import { valuesOf } from "../../utils/CoreUtils";
import { setWorldFlag } from "../Flags";
import { GameState, GameStateTransform } from "../GameState";
import { getCurrentEnemyHP } from "../exploration/Exploration";
import { ExplorationStatus } from "../exploration/ExplorationStatus";
import { getExplorationStatus } from "../exploration/ExplorationUtils";
import { getCurrentEnemy } from "../exploration/enemies/Enemies";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { EmptyEventMessage } from "./EmptyEventMessage";
import {
  EventMessage,
  EventMessageOption,
  EventTag,
  GameEvent,
} from "./GameEvent";

const eventMessages: Record<string, EventMessage> = {};

export function registerEventMessage(message: EventMessage) {
  eventMessages[message.getId()] = message;
}

export function eventMessageExists(id: string): boolean {
  return Boolean(eventMessages?.[id]);
}

export function getEventMessageById(id: string): EventMessage {
  return (eventMessages?.[id] || EmptyEventMessage) as EventMessage;
}

export type EventOccurrenceData = {
  eventOccurrenceId: string;
  eventId: string;
  currentMessageId: string | null;
  timestamp: number;
  timestampSecondsPlayed: number;
  params: any;
  messageHistory: string[];
};

const gameEvents: Record<string, GameEvent> = {};

export function registerGameEvent(event: GameEvent) {
  gameEvents[event.getId()] = event;
}

export function getGameEventById(id: string): GameEvent {
  return gameEvents?.[id] as GameEvent;
}

export function getAllGameEvents(): Record<string, GameEvent> {
  return gameEvents;
}

export function getEventOccurrenceById(
  state: GameState,
  id: string,
): EventOccurrenceData | undefined {
  return state.run.eventOccurrences?.[id];
}

function executeEventOccurrenceActionImpl(
  occurrenceId: string,
  selectedOption: EventMessageOption,
  state: GameState,
) {
  const nextMessage = selectedOption.nextMessage;
  if (nextMessage) {
    if (state.run.eventOccurrences[occurrenceId].messageHistory == null) {
      state.run.eventOccurrences[occurrenceId].messageHistory = [];
    }
    state.run.eventOccurrences[occurrenceId].messageHistory.push(
      state.run.eventOccurrences[occurrenceId].currentMessageId as string,
    );
    state.run.eventOccurrences[occurrenceId].currentMessageId =
      nextMessage.getId();

    // Do this to avoid accidentally cleaning up events after they go terminal
    if (nextMessage.isTerminal()) {
      state.run.eventOccurrences[occurrenceId].timestamp = Date.now();
      state.run.eventOccurrences[occurrenceId].timestampSecondsPlayed =
        getSecondsPlayed(state);
    }
  }

  if (selectedOption.transform) {
    state = selectedOption.transform(
      state,
      state.run.eventOccurrences[occurrenceId].params,
    );
  }

  return state;
}

export function executeEventOccurrenceAction(
  this: any,
  occurrenceId: string,
  selectedOption: EventMessageOption,
): GameStateTransform {
  return executeEventOccurrenceActionImpl.bind(
    this,
    occurrenceId,
    selectedOption,
  );
}

export function jumpEventToMessageIfExists(
  state: GameState,
  eventId: string,
  messageId: string,
): GameState {
  const eventOccurrence = valuesOf(state.run.eventOccurrences).find(
    (eventOccurrence) => eventOccurrence.eventId == eventId,
  );
  if (eventOccurrence == null) {
    return state;
  }

  eventOccurrence.messageHistory.push(eventOccurrence.currentMessageId);
  eventOccurrence.currentMessageId = eventId + messageId;
  eventOccurrence.timestamp = Date.now();
  state = addOccurrenceToQueueImpl(eventOccurrence.eventOccurrenceId, state);

  return state;
}

function addOccurrenceToQueueImpl(occurrenceId: string, state: GameState) {
  if (!state.run.eventQueue.includes(occurrenceId)) {
    state.run.eventQueue.push(occurrenceId);
  }
  return state;
}

export function addOccurrenceToQueue(
  this: any,
  occurrenceId: string,
): GameStateTransform {
  return addOccurrenceToQueueImpl.bind(this, occurrenceId);
}

function removeOccurrenceFromQueueImpl(occurrenceId: string, state: GameState) {
  const position = state.run.eventQueue.lastIndexOf(occurrenceId);
  if (position >= 0) {
    state.run.eventQueue.splice(position, 1);
  }

  return state;
}

export function removeOccurrenceFromQueue(
  this: any,
  occurrenceId: string,
): GameStateTransform {
  return removeOccurrenceFromQueueImpl.bind(this, occurrenceId);
}

function triggerEventImpl(
  event: GameEvent,
  params: any,
  extraTags: EventTag[],
  state: GameState,
) {
  const totalTags: EventTag[] = [];
  totalTags.push(...event.getTags());
  totalTags.push(...extraTags);

  if (
    totalTags.includes(EventTag.DO_NOT_AUTO_TRIGGER_IF_ONGOING) &&
    !state.run.nextEventIsManuallyTriggered
  ) {
    const isAlreadyOngoing = Object.values(state.run.eventOccurrences).find(
      (occurrenceData) =>
        occurrenceData.eventId == event.getId() &&
        occurrenceData.currentMessageId &&
        !getEventMessageById(occurrenceData.currentMessageId).isTerminal(),
    );
    if (isAlreadyOngoing) {
      return state;
    }
  }

  if (totalTags.includes(EventTag.STORYLINE)) {
    state = setWorldFlag("storyline_event_ever_triggered")(state);
  }

  const occurrenceId = event.getId() + Date.now();
  state.run.eventOccurrences[occurrenceId] = {
    eventOccurrenceId: occurrenceId,
    eventId: event.getId(),
    currentMessageId: event.getMainEventMessage().getId(),
    timestamp: Date.now(),
    timestampSecondsPlayed: getSecondsPlayed(state),
    params,
    messageHistory: [],
  };

  const everTriggered = event.hasEverTriggered(state);
  if (
    totalTags.includes(EventTag.MAIN_QUEST) &&
    state.options.hideEventTags.includes(EventTag.MAIN_QUEST_SEEN_BEFORE) &&
    everTriggered
  ) {
    let currentMessage = getEventMessageById(
      getEventOccurrenceById(state, occurrenceId).currentMessageId,
    );
    while (
      !currentMessage.isTerminal() &&
      currentMessage.getAllowedOptions(state, params).length == 1
    ) {
      state = executeEventOccurrenceActionImpl(
        occurrenceId,
        currentMessage.getAllowedOptions(state, params)[0],
        state,
      );
      currentMessage = getEventMessageById(
        getEventOccurrenceById(state, occurrenceId).currentMessageId,
      );
    }
  }

  const transform = event.onTrigger();
  if (transform) {
    state = transform(state, params);
  }
  if (
    shouldPopUpEventWithTags(
      state,
      event,
      occurrenceId,
      totalTags,
      everTriggered,
    ) ||
    state.run.nextEventIsManuallyTriggered
  ) {
    state.run.eventQueue.unshift(occurrenceId);
  }
  state.run.nextEventIsManuallyTriggered = false;
  return state;
}

export function triggerEvent(
  this: any,
  event: GameEvent,
  params?: any,
  extraTags?: EventTag[],
): GameStateTransform {
  return triggerEventImpl.bind(this, event, params || {}, extraTags ?? []);
}

export function shouldHideEventsWithTag(
  state: GameState,
  tag: EventTag,
): boolean {
  return state.options.hideEventTags.includes(tag);
}

export function shouldPopUpEventWithTags(
  state: GameState,
  event: GameEvent,
  occurrenceId: string,
  tags: EventTag[],
  everTriggered: boolean,
): boolean {
  if (
    tags.includes(EventTag.MAIN_QUEST) &&
    state.options.hideEventTags.includes(EventTag.MAIN_QUEST_SEEN_BEFORE) &&
    everTriggered
  ) {
    const currentMessage = getEventMessageById(
      getEventOccurrenceById(state, occurrenceId).currentMessageId,
    );
    if (currentMessage.isTerminal()) {
      return false;
    }
  }
  if (
    state.options.doNotPopupEventsDuringBossFights &&
    getExplorationStatus(state) == ExplorationStatus.Combat &&
    getCurrentEnemy(state)?.isBoss?.() &&
    getCurrentEnemyHP(state) > 0
  ) {
    return false;
  }
  return (
    !state.options.hideEventTags.some((tag) => tags.includes(tag)) &&
    !tags.includes(EventTag.DO_NOT_POPUP)
  );
}
