import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import {
  getCurrentDungeonFloor,
  modifyPlayerCurrentHP,
} from "../../exploration/Exploration";
import { ExplorationStatus } from "../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../exploration/ExplorationUtils";
import { addToInventory } from "../../items/Inventory";
import { ItemStack } from "../../items/Item";
import { Items } from "../../items/Items";
import { registerRandomEventTrigger } from "../autorandom/RandomEventTrigger";
import { buildEvent } from "../EventMessageBuilder";
import { EventTag, EventTransform, GameEvent } from "../GameEvent";

export function createChestEvent(
  eventId: string,
  dungeonFloorIds: string[],
  weight: number,
  contents: ItemStack[],
): GameEvent {
  const newChestEventBuilder = buildEvent(eventId, "Found a chest", [
    EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
    EventTag.DO_NOT_AUTO_TRIGGER_IF_ONGOING,
  ]);

  newChestEventBuilder
    .message(
      t(
        `While exploring, you find a suspicious chest in the middle of your path. What do you do?`,
      ),
    )
    .option(
      t("Open the chest"),
      {
        transform: (state) => {
          for (let itemStack of contents) {
            const item = Items.getById(itemStack.itemOccurrence.itemId);
            state = addToInventory(
              {
                itemId: item.getId(),
                params: item.getDefaultParams(),
              },
              itemStack.amount,
              state,
            );
          }
          return state;
        },
      },
      "open",
    )
    .option(t("Do not open"), "notOpen")
    .option(t("Decide later"))
    .end();

  newChestEventBuilder
    .message(
      t(
        `You open the chest. There's something inside! You pick it up and take it.
    
  **You obtained the following item(s): {{items}}!**`,
        {
          items: contents
            .map((itemStack) => {
              const item = Items.getById(itemStack.itemOccurrence.itemId);
              return t("{{amount}}{{itemIcon}}{{itemName}}", {
                amount:
                  itemStack.amount > 1
                    ? t("{{amount}} ", {
                        amount: formatNumber(itemStack.amount),
                      })
                    : "",
                itemIcon: `:${item.getId()}:`,
                itemName: item.getName(itemStack.itemOccurrence.params),
              });
            })
            .join(t(", ")),
        },
      ),
    )
    .tag("open")
    .end();

  newChestEventBuilder
    .message(
      t(
        `You decide not to open the chest. You think it might be a trap. Best to be careful...`,
      ),
    )
    .tag("notOpen")
    .end();
  const newChestEvent = newChestEventBuilder.build();

  registerRandomEventTrigger(
    newChestEvent,
    weight,
    (state) =>
      getExplorationStatus(state) != ExplorationStatus.None &&
      dungeonFloorIds.includes(getCurrentDungeonFloor(state)?.getId()),
  );

  return newChestEvent;
}

export function createBadChestEvent(
  eventId: string,
  weight: number,
  lastMessage: string,
  transform: EventTransform,
  dungeonFloorIds?: string[],
): GameEvent {
  const newChestEventBuilder = buildEvent(eventId, "Found a chest", [
    EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
    EventTag.DO_NOT_AUTO_TRIGGER_IF_ONGOING,
  ]);

  newChestEventBuilder
    .message(
      t(
        `While exploring, you find a suspicious chest in the middle of your path. What do you do?`,
      ),
    )
    .option(
      t("Open the chest"),
      {
        transform: transform,
      },
      "open",
    )
    .option(t("Do not open"), "notOpen")
    .option(t("Decide later"))
    .end();

  newChestEventBuilder.message(lastMessage).tag("open").end();
  newChestEventBuilder
    .message(
      t(
        `You decide not to open the chest. You think it might be a trap. Best to be careful...`,
      ),
    )
    .tag("notOpen")
    .end();

  const newChestEvent = newChestEventBuilder.build();

  registerRandomEventTrigger(
    newChestEvent,
    weight,
    (state) =>
      getExplorationStatus(state) != ExplorationStatus.None &&
      (dungeonFloorIds == null ||
        dungeonFloorIds.includes(getCurrentDungeonFloor(state)?.getId())),
  );

  return newChestEvent;
}

export function loadBadChestEvent() {
  createBadChestEvent(
    "badChestEvent",
    15,
    `You open the chest. All of a sudden, you feel *pain* and fall to your knees! When you finally look at yourself, you notice a slight wound - as if something had pierced your leg. The chest must have been a trap! Thankfully the wound doesn't look too serious, but you'll need to wait and recover before you can continue to explore...
      
**You lose all your :hp:!**`,
    (state) => {
      state = modifyPlayerCurrentHP(state, -Infinity, "Chest");
      return state;
    },
  );

  createBadChestEvent(
    "emptyChestEvent",
    20,
    `You open the chest. You look inside, but... there's nothing that you can see. You try to feel around to see if you missed anything, but still, there is nothing. What a letdown...`,
    (state) => {
      return state;
    },
  );
}
