import { useCallback, useEffect, useRef } from "react";
import { serializeSaveData } from "../../backend/SaveData";
import { electron, isElectron } from "../../utils/ElectronUtils";
import { useGameState } from "./useGameState";

export function SaveDataBackupper() {
  if (!isElectron()) {
    return null;
  }

  const state = useGameState();

  const stateRef = useRef(state);
  stateRef.current = state;

  const backupCallback = useCallback(async () => {
    electron().ipcRenderer.send(
      "saveFileBackup",
      await serializeSaveData(stateRef.current),
    );
  }, []);

  useEffect(() => {
    const interval = setInterval(async () => {
      try {
        await backupCallback();
      } catch (e: any) {
        throw e;
      }
    }, 15 * 60 * 1000); // 15 min
    return () => {
      clearTimeout(interval);
    };
  }, [backupCallback]);

  useEffect(() => {
    window.addEventListener("beforeunload", backupCallback);
    return () => window.removeEventListener("beforeunload", backupCallback);
  }, [backupCallback]);

  // Do not render anything
  return null;
}
