import { GameState, GameStateTransform } from "./GameState";

export type GameStateListener = (
  newState: GameState,
) => GameStateTransform | null | undefined | void;

type GameStateListenerTrigger = (
  state: GameState,
  oldValue?: boolean | string | number,
) => boolean | string | number;

const listeners: Record<string, GameStateListener> = {};
const highPriorityListeners: Record<string, GameStateListener> = {};
const triggerExtractors: Record<string, GameStateListenerTrigger> = {};
const triggerValues: Record<string, boolean | string | number> = {};

type PerfInfo = {
  executed: number;
  listenerMs: number;
  executedTrigger: number;
  triggerMs: number;
};
const perfInfo: Record<string, PerfInfo> = {};

export function addGameStateListener(
  id: string,
  listener: GameStateListener,
  triggerExtractor?: GameStateListenerTrigger,
) {
  listeners[id] = listener;
  if (triggerExtractor != null) {
    triggerExtractors[id] = triggerExtractor;
  }
}

export function addHighPriorityGameStateListener(
  id: string,
  listener: GameStateListener,
  triggerExtractor?: GameStateListenerTrigger,
) {
  highPriorityListeners[id] = listener;
  if (triggerExtractor != null) {
    triggerExtractors[id] = triggerExtractor;
  }
}

export function clearGameStateListener(id: string) {
  delete listeners[id];
  delete highPriorityListeners[id];
  delete triggerExtractors[id];
  delete triggerValues[id];
}

function getPerfInfoForListener(key: string): PerfInfo {
  if (perfInfo[key] == null) {
    perfInfo[key] = {
      executed: 0,
      listenerMs: 0,
      executedTrigger: 0,
      triggerMs: 0,
    };
  }
  return perfInfo[key];
}

export function applyHighPriGameStateListeners(
  newState: GameState,
): GameStateTransform[] {
  const validHighPriListeners = Object.keys(highPriorityListeners)
    .filter((listenerKey) => {
      if (triggerExtractors?.[listenerKey] != null) {
        const oldValue = triggerValues?.[listenerKey];
        const beginTimestamp = performance.now();
        const newValue = triggerExtractors[listenerKey](newState, oldValue);
        const endTimestamp = performance.now();
        getPerfInfoForListener(listenerKey).executedTrigger++;
        getPerfInfoForListener(listenerKey).triggerMs +=
          endTimestamp - beginTimestamp;

        if (triggerValues?.[listenerKey] == null) {
          triggerValues[listenerKey] = newValue;
          return false;
        }

        if (newValue == triggerValues?.[listenerKey]) {
          return false;
        }

        triggerValues[listenerKey] = newValue;
      }

      return true;
    })
    .map((listenerKey) => {
      const beginTimestamp = performance.now();
      const result = highPriorityListeners[listenerKey](newState);
      const endTimestamp = performance.now();
      getPerfInfoForListener(listenerKey).executed++;
      getPerfInfoForListener(listenerKey).listenerMs +=
        endTimestamp - beginTimestamp;
      return result;
    });

  return validHighPriListeners.filter(
    (transform) => transform != null,
  ) as GameStateTransform[];
}

export function applyGameStateListeners(
  newState: GameState,
): GameStateTransform[] {
  const validListeners = [];
  for (let listenerKey in listeners) {
    const extractor = triggerExtractors?.[listenerKey];
    if (extractor != null) {
      const oldValue = triggerValues?.[listenerKey];
      // const beginTimestamp = performance.now();
      const newValue = extractor(newState, oldValue);
      // const endTimestamp = performance.now();
      // getPerfInfoForListener(listenerKey).executedTrigger++;
      // getPerfInfoForListener(listenerKey).triggerMs +=
      //   endTimestamp - beginTimestamp;
      if (oldValue == null) {
        triggerValues[listenerKey] = newValue;
        continue;
      }

      if (newValue == oldValue) {
        continue;
      }

      triggerValues[listenerKey] = newValue;
    }

    validListeners.push(listenerKey);
  }
  const listenerResults = [];
  for (let listenerKey of validListeners) {
    // const beginTimestamp = performance.now();
    const result = listeners[listenerKey](newState);
    // const endTimestamp = performance.now();
    // getPerfInfoForListener(listenerKey).executed++;
    // getPerfInfoForListener(listenerKey).listenerMs +=
    //   endTimestamp - beginTimestamp;
    listenerResults.push(result);
  }

  return listenerResults.filter(
    (transform) => transform != null,
  ) as GameStateTransform[];
}

export function applyAllTransforms(
  state: GameState,
  transform: GameStateTransform,
): GameState {
  let transforms = [transform];

  do {
    if (transforms.length > 0) {
      state.global.tickCount += 1;
      for (let trans of transforms) {
        state = trans(state);
      }
    }

    transforms = applyHighPriGameStateListeners(state);
    if (transforms.length == 0) {
      transforms = applyGameStateListeners(state);
    }
  } while (transforms.length > 0);

  // Increment one more time to avoid cache
  state.global.tickCount += 1;

  // Return shallow clone for React to trigger context update
  return Object.assign({}, state);
}

