import memoize from "sonic-memoize";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Flag } from "../Flag";
import { hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../action/ActionAreas";
import { SpellElement } from "../spells/Elements";
import {
  applyTransformations,
  explainTransformationsText,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Building, BuildingSpell } from "./Building";

const BASE_TIME = 30;
const saleStallTags = [TransformationTags.TimePerAutoSalePerStall, "saleStall"];
function getSaleStallSecsPerSale(state: GameState): number {
  return applyTransformations(saleStallTags, state, BASE_TIME);
}
const secsPerSaleUnit = memoize(() => ({
  unit: t(" sec"),
}));
function getSaleStallSecsPerSaleExplanation(state: GameState): string {
  return explainTransformationsText(
    saleStallTags,
    state,
    BASE_TIME,
    secsPerSaleUnit(),
  );
}

class SaleStall extends Building {
  getId(): string {
    return "saleStall";
  }

  getName(): string {
    return t("Market Stall");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A public-facing area of your home used to sell things you have marked for sale.",
    );
  }

  getDisplayEffect(state: GameState): string {
    return t(
      "Sells one item every ^{{amount}}^<{{timeExplanation}}> sec for ^{{multiplier}}x^<{{multiplierExplanation}}> the normal price",
      {
        amount: formatNumber(getSaleStallSecsPerSale(state), {
          showDecimals: true,
        }),
        timeExplanation: getSaleStallSecsPerSaleExplanation(state),
        multiplier: formatNumber(getSaleStallPriceMultiplier(state), {
          showDecimals: true,
        }),
        multiplierExplanation: getSaleStallPriceMultiplierExplanation(state),
      },
    );
  }

  getBaseLandRequired(): number {
    return 3;
  }

  canTurnOff(): boolean {
    return true;
  }
}

const saleStall = new SaleStall();

class BuildSaleStall extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.OTHER] };
  }

  getBuilding(): Building {
    return saleStall;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Coins: 200,
      AirEssence: 400,
      EarthEssence: 100,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      Coins: 1.35,
      AirEssence: 1.35,
      EarthEssence: 1.35,
    };
  }

  getBaseManaCost(state: GameState): number {
    return 10;
  }

  isVisible(state: GameState): boolean {
    return isMarketUnlocked(state) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }
}

export function isMarketUnlocked(state: GameState) {
  return hasFlag(state, Flag.SaleStallsUnlocked);
}

const saleStallRevenueTags = [TransformationTags.SaleStallRevenue];
export function getSaleStallPriceMultiplier(state: GameState): number {
  return applyTransformations(saleStallRevenueTags, state, 2.5);
}

const multiplierUnit = memoize(() => ({
  unit: t("x"),
}));

export function getSaleStallPriceMultiplierExplanation(
  state: GameState,
): string {
  return explainTransformationsText(
    saleStallRevenueTags,
    state,
    2.5,
    multiplierUnit(),
  );
}

const buildSaleStall = new BuildSaleStall();

export { buildSaleStall as BuildSaleStall, saleStall as SaleStall };
