import { t } from "../../../../utils/I18n";
import { getPrimaryElementMaxLevelForRun } from "../../../spells/Elements";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { LevelUpListeners } from "../../progression/LevelUp";
import { Posterity } from "./PosterityStoryline";

export function loadRetireUnlocker() {
  Storylines.register(Posterity);
  const retireUnlockEventBuilder = buildEvent(
    "posterity",
    t("(Storyline) Posterity"),
    [EventTag.STORYLINE],
  ).setStorylineIds(Posterity.getId());

  retireUnlockEventBuilder.message(
    t(`As you continue casting various different kinds of spells, your knowledge becomes more advanced and you start realizing that you are actually learning, unlike when you were at school. You are learning not just about the elements and about their magic, but you are also learning about *how to learn* - how to become more proficient in the process itself.
  
You pull out your diary and intend to write about it, but as you begin to flip through the pages, you notice one where your writing was particularly sloppy. You stop, and the blood freezes in your veins: it is your entry from the day your close relative succumbed to illness.`),
  );
  retireUnlockEventBuilder
    .message(
      t(`You read a slight bit. It has already been several years since then, but you still remember how devastated you were. That specific day, too, was the day when you decided that no one else should go through the horrors they did - the day when you vowed to yourself to find or create the *Philosopher's Stone*. You had then enrolled into magic school, hoping to be able to deduce how to make it, but... it was merely a pipe dream, wasn't it?
  
You take a deep breath and jump over to the last page, and you begin writing about what you have learned so far. You suddenly become quite tired. It feels like such a difficult task - and you don't know how long it would even take. You feel tears welling in your eyes - and soon after, you tell yourself you want to *give up*.`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = Posterity.complete(state);
          state = Posterity.activateBonus(state);
          return state;
        },
      },
      "end",
    );
  retireUnlockEventBuilder
    .message(
      t(`However, you stop yourself. Not yet... no, you can't give up. You have barely started, you tell yourself. But, you recognize the immensity of the task you laid upon yourself, and realize that you are not sure if you'll be able to get to the end like this.
  
Because of that reason, you make one more promise to yourself - you are going to write about everything that you have learnt in this same diary. If you ever decide to give up, someone can then pick up this book, and hopefully that will give them a head start and set them up for success. With renewed energy, you quickly finish what you were writing and continue practicing magic.

**You have permanently unlocked the "Retirement" feature! At any point, you can decide to retire - starting the game over again from the beginning, but obtaining powerful bonuses. You can do so from the "Retirement" screen!**`),
    )
    .tag("end")
    .end();

  const retireUnlockEvent = retireUnlockEventBuilder.build();

  LevelUpListeners.register((state, element) => {
    if (!state.run.primaryElement) {
      return state;
    }
    if (
      !retireUnlockEvent.hasTriggered(state) &&
      !Posterity.isCompleted(state) &&
      getPrimaryElementMaxLevelForRun(state) >= 7
    ) {
      return triggerEvent(retireUnlockEvent)(state);
    }
    return state;
  }, retireUnlockEvent.getId());
}
