import { keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { getUnlockedElements, SpellElement } from "../spells/Elements";
import {
  applyTransformationsCached,
  applyTransformationsPermaCached,
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Ritual } from "./Ritual";
import { loadRitualWeakness } from "./RitualWeakness";

export const Rituals = new DataStore<Ritual>();

export function getActiveRituals(state: GameState): Ritual[] {
  return keysOf(state.run.activeRituals)
    .filter((ritualId) => state.run.activeRituals[ritualId])
    .map((id) => Rituals.getById(id));
}

const CORRUPTION_CAP_TAGS = [TransformationTags.CorruptionCap];
export function getCorruptionCap(state: GameState): number {
  return applyTransformationsCached(CORRUPTION_CAP_TAGS, state, 1);
}

const CORRUPTION_TAGS = [
  TransformationTags.Corruption,
  TransformationTags.Accumulation,
];
export function getCorruption(state: GameState): number {
  return applyTransformationsPermaCached(CORRUPTION_TAGS, state, 0);
}

const CORRUPTION_HP_PENALTY_BASE = 0.9;
const CORRUPTION_HP_PENALTY_PER_CORRUPTION = 1.0;
const CORRUPTION_HP_PENALTY_OVER_CAP = 0.00001;
export function isCorruptionHPPenaltyActive(state: GameState) {
  return (
    getCorruption(state) >=
    getCorruptionCap(state) + CORRUPTION_HP_PENALTY_OVER_CAP
  );
}

export function getCorruptionHPPenaltyMultiplier(state: GameState): number {
  if (!isCorruptionHPPenaltyActive(state)) {
    return 1.0;
  }
  return Math.pow(
    CORRUPTION_HP_PENALTY_BASE,
    Math.max(getCorruption(state) - getCorruptionCap(state), 0) /
      CORRUPTION_HP_PENALTY_PER_CORRUPTION,
  );
}

const CORRUPTION_EXP_PENALTY_BASE = 0.7;
const CORRUPTION_EXP_PENALTY_PER_CORRUPTION = 3.0;
const CORRUPTION_EXP_PENALTY_OVER_CAP = 3.0;
export function isCorruptionExpPenaltyActive(state: GameState) {
  return (
    getCorruption(state) >=
    getCorruptionCap(state) + CORRUPTION_EXP_PENALTY_OVER_CAP
  );
}

export function getCorruptionExpPenaltyMultiplier(state: GameState): number {
  if (!isCorruptionExpPenaltyActive(state)) {
    return 1.0;
  }
  return Math.pow(
    CORRUPTION_EXP_PENALTY_BASE,
    Math.max(getCorruption(state) - getCorruptionCap(state), 0) /
      CORRUPTION_EXP_PENALTY_PER_CORRUPTION,
  );
}

const CORRUPTION_TRANSMUTE_PENALTY_BASE = 2.0;
const CORRUPTION_TRANSMUTE_PENALTY_PER_CORRUPTION = 5.0;
const CORRUPTION_TRANSMUTE_PENALTY_OVER_CAP = 5.0;
export function isCorruptionTransmutePenaltyActive(state: GameState) {
  return (
    getCorruption(state) >=
    getCorruptionCap(state) + CORRUPTION_TRANSMUTE_PENALTY_OVER_CAP
  );
}

export function getCorruptionTransmutePenaltyMultiplier(
  state: GameState,
): number {
  if (!isCorruptionTransmutePenaltyActive(state)) {
    return 1.0;
  }
  return Math.pow(
    CORRUPTION_TRANSMUTE_PENALTY_BASE,
    Math.max(getCorruption(state) - getCorruptionCap(state), 0) /
      CORRUPTION_TRANSMUTE_PENALTY_PER_CORRUPTION,
  );
}

export function areRitualsUnlocked(state: GameState): boolean {
  return (
    getUnlockedElements(state).includes(SpellElement.Death) &&
    hasFlag(state, Flag.RitualsUnlocked)
  );
}

export function areRitualsEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.RitualsUnlocked);
}

export function getVisibleRituals(state: GameState): Ritual[] {
  return hasFlag(state, Flag.RitualsUnlocked)
    ? Rituals.getAll().filter((ritual) => ritual.isVisible(state))
    : [];
}

export function loadRitualCommonEffects() {
  loadRitualWeakness();

  // Corruption implementation
  registerTransformation(
    [[TransformationTags.Corruption, TransformationTags.Accumulation]],
    "ritualsCorruption",
    t("Active Rituals"),
    TransformationType.Addition,
    (state) => {
      const activeRituals = getActiveRituals(state);
      return activeRituals
        .map((ritual) => ritual.getCorruptionCost(state))
        .reduce((prev, current) => prev + current, 0);
    },
  );

  // Corruption negative effects
  registerTransformation(
    [[AttackTarget.Player, CombatStat.MaxHP]],
    "corruptionHP",
    t("Corruption"),
    TransformationType.Multiplier,
    (state) => getCorruptionHPPenaltyMultiplier(state),
  );
  registerTransformation(
    [[TransformationTags.ExpGain]],
    "corruptionExp",
    t("Corruption"),
    TransformationType.Multiplier,
    (state) => getCorruptionExpPenaltyMultiplier(state),
  );
  registerTransformation(
    [[TransformationTags.ItemCost], [TransformationTags.ItemCostItem]],
    "corruptionTransmute",
    t("Corruption"),
    TransformationType.Multiplier,
    (state) => getCorruptionTransmutePenaltyMultiplier(state),
  );
}
