import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";

class MonstiumCollection extends Enhancement {
  getId(): string {
    return "monstiumCollection";
  }
  getBaseName(): string {
    return t("Monstium Collection");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_EXPLORATION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 400,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 4,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t("Enhance your ability to collect loot from defeated enemies.");
  }
  getDisplayEffect(state: GameState): string {
    return t("+40%:monstium: obtained from Exploration");
  }
}

export const monstiumCollection = new MonstiumCollection();

registerTransformation(
  [
    [
      TransformationTags.Loot,
      TransformationTags.FromExploration,
      Resource.Monstium,
    ],
  ],
  monstiumCollection.getId(),
  monstiumCollection.getBaseName(),
  TransformationType.Multiplier,
  (state) => (monstiumCollection.isPurchased(state) ? 1.4 : 1.0),
);
