import { t } from "../../../../utils/I18n";
import { ManaSpout } from "../../../buildings/mana/ManaSpout";
import { trainingTowers } from "../../../exploration/dungeons/training/TrainingTowers";
import { getCurrentDungeonFloor } from "../../../exploration/Exploration";
import { ExplorationStatus } from "../../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../../exploration/ExplorationUtils";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { getElementLevel, SpellElement } from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { registerRandomEventTrigger } from "../../autorandom/RandomEventTrigger";
import { buildEvent } from "../../EventMessageBuilder";
import { EventTag } from "../../GameEvent";

const advancedManaSpoutsStoryline = new Storyline(
  "advancedManaSpouts",
  t("Advanced Mana Spouts"),
  130,
  10,
  (state) => t("Explore the Training Towers"),
  t("+30% effectivity and -30% cost scaling of Mana Spouts"),
  (_state) => true,
);

export function loadAdvancedManaSpouts() {
  Storylines.register(advancedManaSpoutsStoryline);

  const advancedManaSpoutsEventBuilder = buildEvent(
    "advancedManaSpouts",
    t("(Storyline) Advanced Mana Spouts"),
    [EventTag.STORYLINE],
  ).setStorylineIds(advancedManaSpoutsStoryline.getId());

  advancedManaSpoutsEventBuilder.message(
    t(`Continuing to climb the Tower of Training and fend enemies left and right, you happen across someone else. It is your old teacher from Wizardry Furniture! His large figure always stood out, easily towering over everyone else in the class. You greet him, and he waves at you.
    
You hold some conversation. He appears interested in how you are setting up your furniture at home. You tell him about your Mana Spouts and how you build them. But then he makes a sour face. Did you say something wrong?`),
  );
  advancedManaSpoutsEventBuilder
    .message(
      t(
        `He explains that you're doing well with your home, but he realized that you are using very basic construction techniques for the Mana Spouts. You've just been basing these off what you could deduce from your own notes, which you know were quite bad, so you are not too surprised.
      
He agrees to teach you how to improve on it, but before that - he mentions that you are no longer a student at the school, and that you will have to both show him that you have the skill to understand the lesson, as well as pay him for the efforts.

You decide to think about it...`,
      ),
    )
    .option(
      t("Take the lesson (8000 Coins, Requires Lv10 Water and Lv10 Air)"),
      {
        transform: (state) => {
          state = grantResource(Resource.Coins, -8000)(state);
          return state;
        },
        isEnabled: (state) => {
          return (
            getResourceAmount(state, Resource.Coins) >= 8000 &&
            getElementLevel(state, SpellElement.Water) >= 10 &&
            getElementLevel(state, SpellElement.Air) >= 10
          );
        },
      },
      "end",
    )
    .option(t("Maybe later"))
    .end();

  advancedManaSpoutsEventBuilder
    .message(
      t(
        `You decide to take the lesson, and hand over the Coins he asked for. He appears satisfied, and guides you to a small room in the Tower that looks like a library. The shelves are lined up with books about furniture. He pulls out a single one, titled: "Mana Spout Construction".
        
Sitting down at a table with you, he shows you some diagrams in the book and explains how the construction method differs. You are definitely able to understand this explanation - everything makes sense. This is how you should have built the Mana Spouts to begin with.`,
      ),
    )
    .option(
      "Continue",
      {
        transform: (state) => advancedManaSpoutsStoryline.complete(state),
      },
      "final",
    )
    .tag("end")
    .end();
  advancedManaSpoutsEventBuilder
    .message(
      t(
        `After the lesson, you head back home. Your teacher said it was ok for you to take the book, so you store it in your bookshelf. It felt like a worthwhile lesson.

You are not sure if you are going to apply it - after all, it is quite a bit of trouble to implement. It is probably something that had to be done from the beginning. Regardless, if the chance ever comes up at some point, now you have a more effective way of doing it.

**You have completed the "Advanced Mana Spouts" Storyline! In future runs, Mana Spouts will produce 30% more Mana and their cost will scale 30% slower!**`,
      ),
    )
    .tag("final");

  const advancedManaSpoutsEvent = advancedManaSpoutsEventBuilder.build();

  registerTransformation(
    [[TransformationTags.Production, ManaSpout.getId()]],
    "advancedManaSpoutsProduction",
    advancedManaSpoutsStoryline.getName(),
    TransformationType.Multiplier,
    (state) => (advancedManaSpoutsStoryline.isBonusActive(state) ? 1.3 : 1.0),
  );

  registerTransformation(
    [[TransformationTags.CostScale, ManaSpout.getId()]],
    "advancedManaSpoutsCostScale",
    advancedManaSpoutsStoryline.getName(),
    TransformationType.Multiplier,
    (state) => (advancedManaSpoutsStoryline.isBonusActive(state) ? 0.7 : 1.0),
  );

  registerRandomEventTrigger(
    advancedManaSpoutsEvent,
    40,
    (state) =>
      !advancedManaSpoutsStoryline.isCompleted(state) &&
      !advancedManaSpoutsEvent.hasTriggered(state) &&
      getExplorationStatus(state) != ExplorationStatus.None &&
      trainingTowers.getDungeonFloors().includes(getCurrentDungeonFloor(state)),
  );
}

export { advancedManaSpoutsStoryline };
