import { keysOf, valuesOf } from "../../../utils/CoreUtils";
import { t } from "../../../utils/I18n";
import { ActionArea } from "../../action/ActionAreas";
import { createHypotheticalState, GameState } from "../../GameState";
import { addGameStateListener } from "../../GameStateListeners";
import { DataStore } from "../../generic/DataStore";
import {
  getVisibleTransmutationSpells,
  isTransmutationUnlocked,
  TransmutationSpell,
} from "../../items/transmute/TransmutationSpell";
import { Ritual } from "../../rituals/Ritual";
import { getVisibleRituals } from "../../rituals/Rituals";
import { LEVELS_PER_RANK } from "../../spells/ElementMultipliers";
import {
  getElementExp,
  getElementLevel,
  getRankForLevel,
  grantElementExp,
  SpellElement,
  translateElement,
} from "../../spells/Elements";
import { Spell } from "../../spells/Spell";
import { getVisibleSpellsForElement } from "../../spells/Spells";
import { clearVisibleSpellsCache } from "../../spells/VisibleSpellsCache";
import { buildEvent } from "../EventMessageBuilder";
import { triggerEvent } from "../Events";
import { EventTag } from "../GameEvent";

export function getConcatenatedRequirements(spell: Spell): string {
  return formatRequirements(spell.getLevelRequirements());
}

function formatRequirements(
  elementRequirements: Partial<Record<SpellElement, number>>,
): string {
  let requirements = [];
  for (let element of keysOf(elementRequirements)) {
    requirements.push(
      t("{{elementName}} Lv{{level}}", {
        elementName: translateElement(element),
        level: elementRequirements[element],
      }),
    );
  }
  return requirements.join(t(", "));
}

export function getMarkdownTextForSpell(spell: Spell): string {
  return `*${spell.getSpellName()}* (${getConcatenatedRequirements(spell)})`;
}

export function getMarkdownTextForSpellEmpowerment(spell: Spell): string {
  return `*${spell.getSpellName()}+* (${formatRequirements(
    spell.getEmpoweringLevelRequirements(),
  )})`;
}

export function getMarkdownTextForTransmutationSpell(
  state: GameState,
  spell: TransmutationSpell,
): string {
  return t("{{itemIcon}} *{{itemName}}* ({{requirements}})", {
    itemIcon: `:${spell.getItem().getId()}:`,
    itemName: spell.getItem().getName(spell.getItem().getDefaultParams()),
    requirements: getConcatenatedRequirements(spell),
  });
}

function getLevelUpLearnedSpellsText(
  oldSpells: Spell[],
  newSpells: Spell[],
): string {
  let diffSpells = newSpells.filter((spell) => !oldSpells.includes(spell));
  let newSpellText = "";
  let extraSpellText = "";
  if (diffSpells.length > 3) {
    const extraSpells = diffSpells.length - 3;
    extraSpellText = t("and {{extraSpells}} more!", { extraSpells });
    diffSpells = diffSpells.slice(0, 3);
  }
  if (diffSpells.length > 0) {
    let newSpellsLearnedText = diffSpells
      .map(getMarkdownTextForSpell)
      .join(t(", "));
    if (extraSpellText !== "") {
      newSpellsLearnedText = [newSpellsLearnedText, extraSpellText].join(
        t(". "),
      );
    }
    newSpellText = t(`**New spells learned:** {{newSpellsLearnedText}}`, {
      newSpellsLearnedText,
    });
  }
  return newSpellText;
}

function getLevelUpLearnedTransmutationsForElement(
  state: GameState,
  element: SpellElement,
  oldSpells: TransmutationSpell[],
  newSpells: TransmutationSpell[],
): string {
  let diffSpells = newSpells.filter(
    (spell) =>
      !oldSpells.includes(spell) &&
      (spell.getLevelRequirements()?.[element] ?? 0) > 0,
  );
  let newSpellsText = "";
  if (isTransmutationUnlocked(state) && diffSpells.length > 0) {
    let extraSpellsText = "";
    if (diffSpells.length > 3) {
      const extraSpells = diffSpells.length - 3;
      extraSpellsText = t("and {{extraSpells}} more!", { extraSpells });
      diffSpells = diffSpells.slice(0, 3);
    }
    let newSpellsLearnedText = diffSpells
      .map(getMarkdownTextForTransmutationSpell.bind(null, state))
      .join(", ");
    if (extraSpellsText !== "") {
      newSpellsLearnedText = [newSpellsLearnedText, extraSpellsText].join(
        t(", "),
      );
    }
    newSpellsText = t(
      `**New Transmutations available:**
{{newSpellsLearnedText}}`,
      { newSpellsLearnedText },
    );
  }
  return newSpellsText;
}

function getLevelUpLearnedEmpoweredSpellsText(
  oldSpells: Spell[],
  newSpells: Spell[],
): string {
  let diffSpells = newSpells.filter((spell) => !oldSpells.includes(spell));
  let newSpellText = "";
  let extraSpellText = "";
  if (diffSpells.length > 3) {
    const extraSpells = diffSpells.length - 3;
    extraSpellText = t("and {{extraSpells}} more!", { extraSpells });
    diffSpells = diffSpells.slice(0, 3);
  }
  if (diffSpells.length > 0) {
    let newSpellsLearnedText = diffSpells
      .map(getMarkdownTextForSpellEmpowerment)
      .join(t(", "));
    if (extraSpellText !== "") {
      newSpellsLearnedText = [newSpellsLearnedText, extraSpellText].join(
        t(". "),
      );
    }
    newSpellText = t(`**New Empowerable spells:** {{newSpellsLearnedText}}`, {
      newSpellsLearnedText,
    });
  }
  return newSpellText;
}

function getLevelUpLearnedRitualsText(
  oldRituals: Ritual[],
  newRituals: Ritual[],
): string {
  let diffRituals = newRituals.filter((ritual) => !oldRituals.includes(ritual));
  let newRitualText = "";
  let extraRitualText = "";
  if (diffRituals.length > 3) {
    const extraRituals = diffRituals.length - 3;
    extraRitualText = t("and {{extraRituals}} more!", {
      extraRituals: extraRituals,
    });
    diffRituals = diffRituals.slice(0, 3);
  }
  if (diffRituals.length > 0) {
    let newRitualsLearnedText = diffRituals
      .map(
        (ritual) =>
          `*${ritual.getBaseName()}* (${formatRequirements(
            ritual.getLevelRequirements(),
          )})`,
      )
      .join(t(", "));
    if (extraRitualText !== "") {
      newRitualsLearnedText = [newRitualsLearnedText, extraRitualText].join(
        t(". "),
      );
    }
    newRitualText = t(`**New Rituals:** {{newRitualsLearnedText}}`, {
      newRitualsLearnedText: newRitualsLearnedText,
    });
  }
  return newRitualText;
}

export const LevelUpListeners = new DataStore<
  (state: GameState, element: SpellElement, oldExp: number) => GameState
>();

export function loadLevelUpEvents() {
  for (let element of valuesOf(SpellElement)) {
    const eventBuilder = buildEvent(
      "Level_Up_" + element,
      t("{{element}} Level Up!", { element: translateElement(element) }),
      [EventTag.LEVEL_UP],
    );

    eventBuilder.message(
      t(
        `Congratulations!
      
Your {{element}} level is now /level/!

/rankText/

/levelUpLearnedSpells/

/levelUpLearnedCrafts/

/levelUpLearnedEmpowers/

/levelUpLearnedRituals/`,
        { element: translateElement(element) },
      ),
    );

    const event = eventBuilder.build();

    addGameStateListener(
      "Level_Up_" + element,
      (state) => {
        // Need to clear this cache to get an accurate level up message
        state.temp.spellRequirementsCache = {};
        let lessExpState = createHypotheticalState(state);
        const level = getElementLevel(state, element);
        lessExpState = grantElementExp(
          element,
          -(
            getElementExp(state, element) -
            (state.run.lastComputedLevelUpElementExperience[element] ?? 0)
          ),
        )(lessExpState);
        const oldLevel = getElementLevel(lessExpState, element);
        if (oldLevel == level) {
          // Do nothing
          return;
        }
        // We leveled up!
        lessExpState = clearVisibleSpellsCache(lessExpState);
        state = clearVisibleSpellsCache(state);
        const oldSpells = getVisibleSpellsForElement(
          lessExpState,
          element,
          ActionArea.SPELL_MENU,
        );
        const newSpells = getVisibleSpellsForElement(
          state,
          element,
          ActionArea.SPELL_MENU,
        );
        const levelUpLearnedSpells = getLevelUpLearnedSpellsText(
          oldSpells,
          newSpells,
        );
        const oldTransmutations = getVisibleTransmutationSpells(lessExpState);
        const newTransmutations = getVisibleTransmutationSpells(state);
        const levelUpLearnedTransmutations =
          getLevelUpLearnedTransmutationsForElement(
            state,
            element,
            oldTransmutations,
            newTransmutations,
          );
        const oldEmpoweredSpells = oldSpells.filter((spell) =>
          spell.isEmpoweringPossible(lessExpState),
        );
        const newEmpoweredSpells = newSpells.filter((spell) =>
          spell.isEmpoweringPossible(state),
        );
        const levelUpLearnedEmpowers = getLevelUpLearnedEmpoweredSpellsText(
          oldEmpoweredSpells,
          newEmpoweredSpells,
        );
        const oldRituals = getVisibleRituals(lessExpState);
        const newRituals = getVisibleRituals(state);
        const levelUpLearnedRituals = getLevelUpLearnedRitualsText(
          oldRituals,
          newRituals,
        );
        const rankText =
          level % LEVELS_PER_RANK == 0
            ? t(
                "You have reached a new Rank: *{{rank}}*. Any {{element}} spells you have learned on a previous rank will become 33% stronger!",
                {
                  rank: getRankForLevel(level),
                  element: translateElement(element),
                },
              )
            : "";
        const tags: EventTag[] = [];
        if (
          !levelUpLearnedSpells &&
          !levelUpLearnedTransmutations &&
          !levelUpLearnedEmpowers &&
          !levelUpLearnedRituals &&
          !rankText
        ) {
          tags.push(EventTag.LEVEL_UP_EMPTY);
        }
        if (
          lessExpState.run.maxElementLevelsOnPrevRetirement[element] >= level
        ) {
          tags.push(EventTag.LEVEL_UP_ALREADY_REACHED);
        }
        return (state) => {
          state = triggerEvent(
            event,
            {
              level,
              rankText,
              levelUpLearnedSpells: levelUpLearnedSpells,
              levelUpLearnedCrafts: levelUpLearnedTransmutations,
              levelUpLearnedEmpowers: levelUpLearnedEmpowers,
              levelUpLearnedRituals,
            },
            tags,
          )(state);
          LevelUpListeners.getAll().forEach((listener) => {
            state = listener(
              state,
              element,
              state.run.lastComputedLevelUpElementExperience[element] ?? 0,
            );
          });
          state.run.lastComputedLevelUpElementExperience[element] =
            getElementExp(state, element);
          state.temp.spellRequirementsCache = {};
          return state;
        };
      },
      (state) => getElementLevel(state, element),
    );
  }
}
