import { Entypo } from "@expo/vector-icons";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { StyleSheet, View, useWindowDimensions } from "react-native";
import { TouchableOpacity } from "react-native-gesture-handler";
import {
  Modal,
  Portal,
  Surface,
  ThemeProvider,
  useTheme,
} from "react-native-paper";
import { getThemeByElementAndDarkMode } from "../Themes";
import { buildSpellcraftingVat } from "../backend/buildings/SpellcraftingVat";
import { Spellcraft } from "../backend/spellcraft/Spellcraft";
import {
  assignSpellToVat,
  destroyVat,
  getVisibleSpellcraftsForSpell,
  removeSpellcraftFromVat,
} from "../backend/spellcraft/SpellcraftUtils";
import { Spellcrafts } from "../backend/spellcraft/Spellcrafts";
import { Spells } from "../backend/spells/Spells";
import { keysOf } from "../utils/CoreUtils";
import { formatNumber } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import {
  ItemGrid,
  ItemGridElement,
  ItemGridElementUsage,
} from "./inventory/ItemGrid";
import { SpellcraftDetails } from "./spellcraft/SpellcraftDetails";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import { GameStateFreezer } from "./utility/GameStateFreezer";
import { Markdown } from "./utility/Markdown";
import { SpellSelect } from "./utility/SpellSelect";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { UIContext, useUIStateFromContext } from "./utility/UIContext";
import { GameIcon } from "./utility/icons/GameIcon";
import { useGameState } from "./utility/useGameState";

function SpellcraftRow(props: { vatId: string; index: number }) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const theme = useTheme();

  const [collapsed, setCollapsed] = useState(true);
  const [selectedElement, setSelectedElement] = useState(
    null as ItemGridElement | null,
  );

  const onPressItem = React.useCallback(
    (item: ItemGridElement) => {
      setSelectedElement(item);
    },
    [setSelectedElement],
  );

  const selectedElementRef = useRef(selectedElement);
  selectedElementRef.current = selectedElement;

  const vatId = props.vatId;
  const spellcraftData = state.run.spellcrafts[vatId];
  const spellcraft = Spellcrafts.getByIdNullable(spellcraftData.spellcraftId);

  const spellcraftName = spellcraft != null ? spellcraft.getName() : t("Empty");
  const spellcraftSpell =
    spellcraftData.spellId != null
      ? Spells.getById(spellcraftData.spellId)
      : null;

  const title = t("Vat {{index}} - {{spellcraftName}}", {
    index: formatNumber(props.index + 1),
    spellcraftName,
  });
  const subtitle =
    spellcraftSpell != null
      ? t("On spell: {{spell}}", { spell: spellcraftSpell.getSpellName() })
      : "";

  const uiContext = useContext(UIContext);
  const [_isModalSelectorOpen, setIsModalSelectorOpen] = useUIStateFromContext(
    uiContext,
    "picker_open",
    false,
  );
  const [showingSpellSelector, setShowingSpellSelector] = useState(false);

  const showModal = useCallback(() => {
    setShowingSpellSelector(true);
    setIsModalSelectorOpen(true);
  }, [setShowingSpellSelector, setIsModalSelectorOpen]);
  const hideModal = useCallback(() => {
    setShowingSpellSelector(false);
    setIsModalSelectorOpen(false);
  }, [setShowingSpellSelector, setIsModalSelectorOpen]);

  const [confirmDestroy, setConfirmDestroy] = useState(false);
  const destroyVatCallback = useCallback(() => {
    if (confirmDestroy) {
      stateRef.current.apply((state) => {
        state = destroyVat(state, vatId);
        buildSpellcraftingVat._demolishUnsafe(state, 1);
        return state;
      });
    } else {
      setConfirmDestroy(true);
    }
  }, [confirmDestroy]);

  const setSpellCallback = useCallback(
    (spellId: string) => {
      stateRef.current.apply((state) =>
        assignSpellToVat(state, vatId, spellId),
      );
      hideModal();
    },
    [vatId, hideModal],
  );

  const removeSpellcraftCallback = useCallback(() => {
    stateRef.current.apply((state) => removeSpellcraftFromVat(state, vatId));
  }, [vatId]);

  const spellcrafts =
    spellcraftSpell != null
      ? getVisibleSpellcraftsForSpell(state, spellcraftSpell)
      : [];

  const itemGridElements = useMemo(
    () =>
      spellcraftSpell != null && spellcraft == null
        ? spellcrafts.map((spellcraft) => ({
            itemOrSpellId: spellcraft.getBaseItem().getId(),
            amount: 1,
            params: spellcraft.getBaseItem().getDefaultParams(),
            disabled: !spellcraft.isSpellCompatible(spellcraftSpell),
          }))
        : [],
    [
      spellcrafts.map((spellcraft) => spellcraft.getId()).join(),
      spellcraftSpell,
      spellcraft,
    ],
  );

  const itemGridElementsJSON = useMemo(
    () => JSON.stringify(itemGridElements),
    [itemGridElements],
  );

  useEffect(() => setSelectedElement(undefined), [itemGridElementsJSON]);

  const selectedSpellcraft =
    selectedElement != null
      ? (Spellcrafts.getAll().find(
          (spellcraft) =>
            spellcraft.getBaseItem().getId() == selectedElement?.itemOrSpellId,
        ) as Spellcraft)
      : undefined;

  const itemGrid = useMemo(
    () => (
      <ItemGrid
        items={itemGridElements}
        location={ItemGridElementUsage.SPELLCRAFT}
        onPressItem={onPressItem}
      />
    ),
    [itemGridElementsJSON, onPressItem],
  );

  const { height, width } = useWindowDimensions();

  const modal = useMemo(
    () => (
      <Modal
        key="changeSpell"
        visible={showingSpellSelector}
        onDismiss={hideModal}
        contentContainerStyle={[
          styles.overlay,
          {
            maxWidth: Math.min(width - 16, 640),
            maxHeight: height - 32,
            backgroundColor: theme.colors.background,
          },
        ]}
      >
        <View style={[Styles.flex, Styles.p16]}>
          <SpellSelect
            onSelect={setSpellCallback}
            spellId={spellcraftData.spellId}
          />
        </View>
      </Modal>
    ),
    [
      showingSpellSelector,
      hideModal,
      width,
      height,
      theme,
      setSpellCallback,
      spellcraftData.spellId,
    ],
  );

  const elementTheme =
    spellcraftSpell != null
      ? getThemeByElementAndDarkMode(spellcraftSpell.getElement(), theme.dark)
      : theme;

  const backgroundColor = elementTheme.colors.secondaryContainer;

  const icon = useMemo(
    () =>
      spellcraft != null ? (
        <View
          style={[styles.tile, { borderColor: elementTheme.colors.outline }]}
        >
          <GameIcon icon={spellcraft.getBaseItem().getId()} />
        </View>
      ) : (
        <View
          style={[styles.tile, { borderColor: elementTheme.colors.outline }]}
        ></View>
      ),
    [spellcraft, elementTheme],
  );

  return useMemo(
    () => (
      // @ts-ignore It works
      <ThemeProvider theme={elementTheme}>
        <Surface
          mode="flat"
          key={props.vatId}
          style={[styles.rowContainer, { backgroundColor }]}
        >
          <TouchableOpacity
            onPress={() => setCollapsed(!collapsed)}
            style={styles.expandTouchable}
          >
            <View style={Styles.flexRowSpaceBetween}>
              <View style={Styles.flexRowAlignCenter}>
                {icon}
                <View style={Styles.ml8}>
                  <Text h4 style={Styles.bold}>
                    {title}
                  </Text>
                  {subtitle != "" && <Text>{subtitle}</Text>}
                </View>
              </View>
              <Entypo
                name={collapsed ? "chevron-right" : "chevron-down"}
                size={24}
                color={elementTheme.colors.onBackground}
              />
            </View>
          </TouchableOpacity>
          <GameStateFreezer isVisible={!collapsed}>
            <View style={collapsed ? { display: "none" } : null}>
              <View style={Styles.mt16}>
                <View style={Styles.flexRowSpaceBetween}>
                  <Markdown>
                    {t("Spell: **{{spell}}**", {
                      spell: spellcraftSpell?.getSpellName?.() ?? t("None"),
                    })}
                  </Markdown>
                  <Button onPress={showModal} compact={true}>
                    {t("Select spell")}
                  </Button>
                  <Portal>{modal}</Portal>
                </View>
              </View>
              {spellcraftSpell != null && (
                <View style={Styles.mt16}>
                  <View style={Styles.flexRowAlignCenter}>
                    <View style={[Styles.flex, Styles.mr16]}>
                      <Markdown>
                        {spellcraft != null
                          ? t("Spellcraft: **{{spellcraft}} - {{effect}}**", {
                              spellcraft: spellcraftName,
                              effect: spellcraft.getEffect(state),
                            })
                          : t("Spellcraft: **Empty**")}
                      </Markdown>
                    </View>
                    {spellcraft != null && (
                      <Button onPress={removeSpellcraftCallback} compact={true}>
                        {t("Remove")}
                      </Button>
                    )}
                  </View>
                  {itemGridElements.length == 0 && spellcraft == null && (
                    <View style={Styles.mt8}>
                      <Text style={{ fontStyle: "italic" }}>
                        {t(
                          "You have no known Spellcrafts that could affect this spell!",
                        )}
                      </Text>
                    </View>
                  )}
                  {itemGridElements.length > 0 && (
                    <View style={Styles.mt8}>{itemGrid}</View>
                  )}
                  {selectedElement != null && (
                    <View style={Styles.mt8}>
                      <SpellcraftDetails
                        vatId={vatId}
                        spellcraftId={selectedSpellcraft.getId()}
                      />
                    </View>
                  )}
                </View>
              )}
              <View style={[Styles.mt16, Styles.ph8]}>
                <CategoryContainer
                  title={t("Destroy Vat")}
                  initiallyExpanded={false}
                  dividerColor={elementTheme.colors.outline}
                >
                  <View style={Styles.mt8}>
                    <Markdown>
                      {t(
                        "If you want to recover the land used by a Spellcrafting Vat, you can destroy the vat here. If you have an active Spellcraft, you will regain the items you used.",
                      )}
                    </Markdown>
                    <View style={[Styles.mt8, Styles.flexCenter]}>
                      <Button onPress={destroyVatCallback}>
                        {confirmDestroy
                          ? t("Press again to confirm")
                          : t("Destroy Vat")}
                      </Button>
                    </View>
                  </View>
                </CategoryContainer>
              </View>
            </View>
          </GameStateFreezer>
        </Surface>
      </ThemeProvider>
    ),
    [
      collapsed,
      setCollapsed,
      elementTheme,
      title,
      subtitle,
      icon,
      !collapsed && spellcraftSpell,
      !collapsed && hideModal,
      !collapsed && spellcraft?.getEffect?.(state),
      !collapsed && spellcraftName,
      !collapsed && selectedElement,
      !collapsed && JSON.stringify(spellcraftData),
      !collapsed && destroyVatCallback,
      !collapsed && removeSpellcraftCallback,
      !collapsed && confirmDestroy,
      elementTheme,
      modal,
    ],
  );
}

export function SpellcraftsComponent() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const vatIds = keysOf(state.run.spellcrafts);

  return useMemo(() => {
    return (
      <View style={styles.topContainer}>
        {vatIds.length > 0 ? (
          <View>
            {vatIds.map((vatId, index) => (
              <View key={vatId} style={Styles.pv4}>
                <SpellcraftRow index={index} vatId={vatId} key={vatId} />
              </View>
            ))}
          </View>
        ) : (
          <Markdown>
            {t(
              "In this screen, you can do Spellcrafting to modify the effects of your spells. However, you have no vats right now, which are needed to do so. Build a Spellcrafting Vat in your Home to get one!",
            )}
          </Markdown>
        )}
      </View>
    );
  }, [vatIds.join()]);
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
  },
  contentContainer: {
    paddingVertical: 8,
    paddingHorizontal: 4,
    alignItems: "stretch",
    justifyContent: "flex-start",
  },
  hintProgress: {
    marginTop: 4,
  },
  loadoutsContainer: {
    borderTopWidth: 1,
    paddingVertical: 8,
    paddingHorizontal: 4,
  },
  categoryTitle: {
    marginLeft: 8,
  },
  categoryContainer: {
    paddingTop: 4,
    marginBottom: 8,
  },
  campusContainer: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
  },
  campusHintWarning: {
    marginTop: 16,
  },
  optionRow: {
    marginTop: 8,
    flexDirection: "row",
    flexWrap: "wrap",
    gap: 8,
    justifyContent: "space-around",
  },
  overlay: {
    alignSelf: "center",
    marginHorizontal: 16,
    maxWidth: 640,
  },
  messageLogContainer: {
    padding: 8,
    borderColor: "#444",
    borderWidth: 1,
    maxHeight: 320,
  },
  rowContainer: {
    padding: 8,
    borderRadius: 8,
  },
  topContainer: {
    gap: 8,
  },
  expandTouchable: {
    // @ts-ignore This is a valid property
    cursor: "pointer",
  },
  tile: {
    borderWidth: 0.5,
    height: 32,
    width: 32,
    justifyContent: "center",
    alignItems: "center",
  },
});
