import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { GameState } from "../GameState";
import { SpellElement, translateElement } from "../spells/Elements";
import { registerCanCastSpellClearer } from "../spells/Spells";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class SilenceElementBase extends TemporaryEffect {
  constructor(element: SpellElement) {
    super();
    this.element = element;
  }
  element: SpellElement;
  getId(): string {
    return "elementalSilence" + this.element;
  }
  getDisplayName(): string {
    return t("Silence {{element}}", {
      element: translateElement(this.element),
    });
  }
  isBeneficial(): boolean {
    return false;
  }
  isCombatOnly(): boolean {
    return true;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Prevented from using {{element}} elemental spells", {
      element: translateElement(this.element),
    });
  }
}

export function silenceElementTemporaryEffect(
  element: SpellElement,
): SilenceElementBase {
  return TemporaryEffects.getById(
    "elementalSilence" + element,
  ) as SilenceElementBase;
}

export function loadElementalSilence() {
  for (let element of valuesOf(SpellElement)) {
    TemporaryEffects.register(new SilenceElementBase(element));
    registerTransformation(
      [[TransformationTags.CanCastSpell, element]],
      "elementalSilence" + element,
      t("Silence {{element}}", { element: translateElement(element) }),
      TransformationType.Override,
      (state, params) => {
        return hasTemporaryEffect(
          state,
          "elementalSilence" + element,
          AttackTarget.Player,
        )
          ? 0
          : undefined;
      },
    );
    registerCanCastSpellClearer("elementalSilence" + element, (state) =>
      hasTemporaryEffect(
        state,
        "elementalSilence" + element,
        AttackTarget.Player,
      ),
    );
  }
}
