let lastSelectedEnemyAction: BattlerAction | undefined = undefined;
let lastSelectedFamiliarAction: BattlerAction | undefined = undefined;

export function isExplorationUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.ExplorationUnlocked);
}

export function isExplorationEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.ExplorationUnlocked);
}

export function startExplorationOnFloor(
  state: GameState,
  dungeonFloor: DungeonFloor,
): GameState {
  if (!state.run.exploration) {
    state.run.exploration = EXPLORATION_INITIAL_STATE;
  }
  if (!dungeonFloor.isUnlocked(state)) {
    return state;
  }
  state.run.exploration.currentDungeonFloorId = dungeonFloor.getId();
  return startExploration(state);
}

export function startExploration(state: GameState): GameState {
  if (!state.run.exploration) {
    state.run.exploration = EXPLORATION_INITIAL_STATE;
  }
  state.run.exploration.explorationStartTime = getSecondsPlayed(state);
  state.run.exploration.explorationStatus = ExplorationStatus.Exploring;
  return state;
}

export function startBossExploration(state: GameState): GameState {
  if (!state.run.exploration) {
    state.run.exploration = EXPLORATION_INITIAL_STATE;
  }
  state.run.exploration.explorationStartTime = getSecondsPlayed(state);
  state.run.exploration.explorationStatus = ExplorationStatus.ExploringBoss;
  return state;
}

export function getExplorationStartTime(state: GameState): number {
  return state.run.exploration?.explorationStartTime || 0;
}

export function getCurrentDungeonFloor(state: GameState): DungeonFloor {
  return DungeonFloors.getById(
    state.run.exploration?.currentDungeonFloorId || "",
  );
}

function startCombatImpl(enemy: Enemy, state: GameState): GameState {
  if (!state.run.exploration) {
    state.run.exploration = EXPLORATION_INITIAL_STATE;
  }
  state = clearCombatTemporaryEffects(state);
  state = resetAllCombatVariables(state);
  for (let itemOccurrence of getEquippedItems(state, EquipmentSlot.Pouch)) {
    if (itemOccurrence != null) {
      const item = Items.getById(itemOccurrence.itemId) as PouchItem;
      state = item.doCleanup(state, itemOccurrence.params);
    }
  }
  state.run.exploration.pouchItemsUsesLeft = {};
  state.run.exploration.explorationStatus = ExplorationStatus.Combat;
  state.run.exploration.currentEnemy = {
    id: enemy.getId(),
    currentHP: enemy.getMaxHP(state),
  };
  state = clearCombatStatCache(state, AttackTarget.Enemy);
  // Do this one more time as getMaxHP depends on currentEnemy being set
  state.run.exploration.currentEnemy.currentHP = enemy.getMaxHP(state);
  state = enemy.onEncounter(state);
  lastSelectedEnemyAction = enemy.getNextAction(state);
  lastSelectedFamiliarAction = undefined;
  const hasCompanion = hasCurrentActiveCompanion(state);
  if (hasCompanion) {
    const companion = getCurrentCompanionClass(state) as FamiliarClass;
    lastSelectedFamiliarAction = companion.getNextActionForCompanion(state);
    state.run.exploration.familiarActionProgress = 0.0;
  }
  state.run.exploration.playerActionProgress = 0.0;
  state.run.exploration.enemyActionProgress = 0.0;
  return state;
}

export function getPlayerActionProgress(state: GameState): number {
  return state.run.exploration?.playerActionProgress || 0.0;
}

export function getEnemyActionProgress(state: GameState): number {
  return state.run.exploration?.enemyActionProgress || 0.0;
}

export function getFamiliarActionProgress(state: GameState): number {
  return state.run.exploration?.familiarActionProgress || 0.0;
}

export function startCombat(this: any, enemy: Enemy): GameStateTransform {
  return startCombatImpl.bind(this, enemy);
}

export function getCurrentPlayerHP(state: GameState): number {
  return state.run.exploration?.currentHP || 0;
}

export function getCurrentFamiliarHP(state: GameState): number {
  return getCurrentCompanionData(state)?.status?.currentHp || 0;
}

export function getCurrentEnemyHP(state: GameState): number {
  return state.run.exploration?.currentEnemy?.currentHP || 0;
}

export function getCurrentPlayerHPFraction(state: GameState): number {
  return getCurrentPlayerHP(state) / calculatePlayerMaxHP(state);
}

export function getCurrentFamiliarHPFraction(state: GameState): number {
  return (
    getCurrentFamiliarHP(state) /
    getCurrentCompanionClass(state).getStatsForCompanion(state).maxHP
  );
}

export function getCurrentEnemyHPFraction(state: GameState): number {
  return (
    getCurrentEnemyHP(state) / (getCurrentEnemy(state)?.getMaxHP(state) ?? 1.0)
  );
}

const DAMAGE_INFLECTION = 1.15;
export function calculateDamage(attack: number, defense: number): number {
  let damage = 1;
  if (attack * DAMAGE_INFLECTION > defense) {
    damage = attack - defense / 2;
  } else {
    damage =
      attack -
      (attack * DAMAGE_INFLECTION) / 2 -
      ((Math.pow(attack, 2) * Math.pow(DAMAGE_INFLECTION, 2)) / 2) *
        -1 *
        (1 / defense - 1 / (DAMAGE_INFLECTION * attack));
  }
  return Math.max(damage, 1);
}

export function calculateIsHit(
  accuracyPoints: number,
  dodgePoints: number,
): boolean {
  const ratio = Math.min(accuracyPoints / Math.max(1, dodgePoints), 1.0);
  // This formula creates the following effect:
  // Ratio >= 1: hitOdds is 100%+
  // Ratio == 0.66: hitOdds is about 74%
  // Ratio == 0.5: hitOdds is about 50%
  // Ratio == 0.25: hitOdds is 0%
  const hitOdds = -1.33333 * Math.pow(ratio, 2) + 3 * ratio - 0.66667;
  return Math.random() < hitOdds;
}

export function modifyTargetCurrentHP(
  state: GameState,
  target: AttackTarget,
  amount: number,
  cause?: string | null,
): GameState {
  switch (target) {
    case AttackTarget.Player:
      return modifyPlayerCurrentHP(state, amount, cause);
    case AttackTarget.Familiar:
      return modifyCompanionCurrentHP(state, amount, cause);
    case AttackTarget.Enemy:
      return modifyEnemyCurrentHP(state, amount);
  }
}

export function modifyPlayerCurrentHP(
  state: GameState,
  amount: number,
  cause?: string | null,
): GameState {
  if (
    amount < 0 &&
    state.run.exploration.currentHP + amount <= 0 &&
    !state.run.exploration.triggerPlayerDeath
  ) {
    cause = cause ?? lastSelectedEnemyAction?.name;
    state.run.exploration.lastPlayerDamageCause = cause;
  }
  state.run.exploration.currentHP = Math.min(
    calculatePlayerMaxHP(state),
    Math.max(state.run.exploration.currentHP + amount, 0),
  );
  if (state.run.exploration.currentHP <= 0) {
    state.run.exploration.triggerPlayerDeath = true;
  }
  return state;
}

export function modifyCompanionCurrentHP(
  state: GameState,
  amount: number,
  cause: string | null | undefined,
): GameState {
  if (!hasCurrentActiveCompanion(state)) {
    return state;
  }

  const currentCompanion = getCurrentCompanionClass(state) as FamiliarClass;
  const currentCompanionData = getCurrentCompanionData(state);
  currentCompanionData.status.currentHp = Math.min(
    currentCompanion.getStatsForCompanion(state).maxHP,
    Math.max(currentCompanionData.status.currentHp + amount, 0),
  );
  if (currentCompanionData.status.currentHp <= 0) {
    currentCompanionData.status.triggerFamiliarDeath = true;
  }
  return state;
}

export function modifyEnemyCurrentHP(
  state: GameState,
  amount: number,
): GameState {
  const currentEnemy = getCurrentEnemy(state);
  state.run.exploration.currentEnemy.currentHP = Math.min(
    currentEnemy.getMaxHP(state),
    state.run.exploration.currentEnemy.currentHP + amount,
  );
  return state;
}

export function executePlayerAutoAttack(state: GameState): GameState {
  state = standardPlayerAttackEffect(state, calculatePlayerAttack(state), {
    tags: [TransformationTags.AutoAttack],
  });
  return state;
}

export function executeFamiliarAction(state: GameState): GameState {
  const familiar = getCurrentCompanionData(state);
  if (
    lastSelectedFamiliarAction &&
    familiar != null &&
    familiar.status.companionStatus === FamiliarCompanionStatusType.Combat
  ) {
    state = lastSelectedFamiliarAction.transform(state);
  }
  return state;
}

export function executeEnemyAction(state: GameState): GameState {
  if (lastSelectedEnemyAction) {
    state = lastSelectedEnemyAction.transform(state);
  }
  return state;
}

export function getCurrentHPFromTarget(
  state: GameState,
  target: AttackTarget,
): number {
  switch (target) {
    case AttackTarget.Player:
      return getCurrentPlayerHP(state);
    case AttackTarget.Familiar:
      return getCurrentFamiliarHP(state);
    case AttackTarget.Enemy:
      return getCurrentEnemyHP(state);
  }
}

export function getStatFromTarget(
  state: GameState,
  target: AttackTarget,
  stat: CombatStat,
): number {
  switch (target) {
    case AttackTarget.Player:
      switch (stat) {
        case CombatStat.MaxHP:
          return calculatePlayerMaxHP(state);
        case CombatStat.Attack:
          return calculatePlayerAttack(state);
        case CombatStat.AttackDelay:
          return calculatePlayerAttackDelaySec(state);
        case CombatStat.Defense:
          return calculatePlayerDefense(state);
        case CombatStat.Accuracy:
          return calculatePlayerAccuracyPoints(state);
        case CombatStat.Dodge:
          return calculatePlayerDodgePoints(state);
        case CombatStat.Crit:
          return calculatePlayerCritPoints(state);
        case CombatStat.HPRegen:
          return calculatePlayerHPRegenRatioPerSec(state);
      }
    case AttackTarget.Familiar:
      const familiar = getCurrentCompanionClass(state);
      if (stat === CombatStat.HPRegen) {
        return 0;
      }
      return familiar.getStatsForCompanion(state, stat)[
        stat[0].toLowerCase() + stat.substring(1)
      ];
    case AttackTarget.Enemy:
      const enemy = getCurrentEnemy(state);
      switch (stat) {
        case CombatStat.MaxHP:
          return enemy?.getMaxHP?.(state);
        case CombatStat.Attack:
          return enemy?.getAttack?.(state);
        case CombatStat.AttackDelay:
          return enemy?.getAttackDelay?.(state);
        case CombatStat.Defense:
          return enemy?.getDefense?.(state);
        case CombatStat.Accuracy:
          return enemy?.getAccuracyPoints?.(state);
        case CombatStat.Dodge:
          return enemy?.getDodgePoints?.(state);
        case CombatStat.Crit:
          return enemy?.getCriticalPoints?.(state);
        case CombatStat.HPRegen:
          return 0;
      }
  }
}

export function calculateAttackTarget(state: GameState): AttackTarget {
  if (
    hasCurrentActiveCompanion(state) &&
    !state.run.exploration.familiarIsInBackRow
  ) {
    return AttackTarget.Familiar;
  }
  return AttackTarget.Player;
}

export function calculateIsEnemyHit(
  state: GameState,
  modifiedAccuracyPoints: number,
  forcedTarget?: AttackTarget,
): boolean {
  const target = forcedTarget ?? calculateAttackTarget(state);
  let dodgePoints = getStatFromTarget(state, target, CombatStat.Dodge);
  return calculateIsHit(modifiedAccuracyPoints, dodgePoints);
}

const attackDamageReceivedByTargetTags = memoize((target) => [
  TransformationTags.AttackDamageReceived,
  target,
]);

const critResistanceByTargetTags = memoize((target) => [
  target,
  TransformationTags.CriticalResistance,
]);

export function standardEnemyAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    accuracyAddition?: number;
    critChanceMultiplier?: number;
    forcedTarget?: AttackTarget;
    tags?: string[];
    cause?: string;
    onHit?: (state: GameState, actionResult: CombatActionResult) => GameState;
  },
): GameState {
  const enemy = getCurrentEnemy(state);
  const modifiedAccuracyPoints =
    (enemy.getAccuracyPoints(state) + (options?.accuracyAddition ?? 0.0)) *
    (options?.accuracyMultiplier ?? 1.0);
  const target = options?.forcedTarget ?? calculateAttackTarget(state);
  const isHit = calculateIsEnemyHit(state, modifiedAccuracyPoints, target);
  let damage = 0,
    isCrit = false;
  if (isHit) {
    const critChance =
      (enemy.getCriticalPoints(state) *
        (options?.critChanceMultiplier ?? 1.0)) /
      100.0;
    isCrit = Math.random() < (critChance ?? 0);
    const critMultiplier = 1.5;

    if (isCrit) {
      const critResistance = applyTransformations(
        critResistanceByTargetTags(target),
        state,
        0,
        { target, tags: options?.tags },
      );
      if (Math.random() < critResistance) {
        isCrit = false;
      }
    }
    const defense = getStatFromTarget(state, target, CombatStat.Defense);
    damage = calculateDamage(
      isCrit ? attack * critMultiplier : attack,
      isCrit ? 0 : defense,
    );
    damage = applyTransformations(
      attackDamageReceivedByTargetTags(target),
      state,
      damage,
      {
        target,
        tags: options?.tags,
      },
    );
    state = modifyTargetCurrentHP(state, target, -damage, options?.cause);
  }
  const enemyAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Enemy,
    target,
    tags: options?.tags ?? [],
  };
  state = pushCombatActionResult(state, enemyAction);
  state = options?.onHit != null ? options.onHit(state, enemyAction) : state;
  return state;
}

export function aoeEnemyAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    accuracyAddition?: number;
    critChanceMultiplier?: number;
    cause?: string;
    tags?: string[];
    onHit?: (state: GameState, actionResult: CombatActionResult) => GameState;
  },
) {
  if (hasCurrentActiveCompanion(state)) {
    state = standardEnemyAttackEffect(state, attack, {
      ...options,
      forcedTarget: AttackTarget.Familiar,
    });
  }
  state = standardEnemyAttackEffect(state, attack, {
    ...options,
    forcedTarget: AttackTarget.Player,
  });
  return state;
}

export function fractionEnemyAttackEffect(
  state: GameState,
  fraction: number,
  options?: {
    accuracyMultiplier?: number;
    forcedTarget?: AttackTarget;
    cause?: string;
    tags?: string[];
  },
): GameState {
  const enemy = getCurrentEnemy(state);
  const modifiedAccuracyPoints =
    enemy.getAccuracyPoints(state) * (options?.accuracyMultiplier ?? 1.0);
  const target = options?.forcedTarget ?? calculateAttackTarget(state);
  const isHit = calculateIsEnemyHit(state, modifiedAccuracyPoints, target);
  const isCrit = false;
  let damage = 0;
  if (isHit) {
    if (target === AttackTarget.Player) {
      damage = getCurrentPlayerHP(state) * fraction;
      state = modifyPlayerCurrentHP(state, -damage, options?.cause);
    } else if (target === AttackTarget.Familiar) {
      damage = getCurrentFamiliarHP(state) * fraction;
      state = modifyCompanionCurrentHP(state, -damage, options?.cause);
    } else {
      damage = getCurrentEnemyHP(state) * fraction;
      state = modifyEnemyCurrentHP(state, -damage);
    }
  }
  const enemyAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Enemy,
    target,
    tags: options?.tags,
  };
  state = pushCombatActionResult(state, enemyAction);
  return state;
}

export function pierceDefenseAoEEnemyAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    critChanceMultiplier?: number;
    cause?: string;
    tags?: string[];
  },
) {
  if (hasCurrentActiveCompanion(state)) {
    state = pierceDefenseEnemyAttackEffect(state, attack, {
      ...options,
      forcedTarget: AttackTarget.Familiar,
    });
  }
  state = pierceDefenseEnemyAttackEffect(state, attack, {
    ...options,
    forcedTarget: AttackTarget.Player,
  });
  return state;
}

export function pierceDefenseEnemyAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    critChanceMultiplier?: number;
    forcedTarget?: AttackTarget;
    cause?: string;
    tags?: string[];
  },
): GameState {
  const enemy = getCurrentEnemy(state);
  const modifiedAccuracyPoints =
    enemy.getAccuracyPoints(state) * (options?.accuracyMultiplier ?? 1.0);
  const target = options?.forcedTarget ?? calculateAttackTarget(state);
  const isHit = calculateIsEnemyHit(state, modifiedAccuracyPoints, target);
  let damage = 0,
    isCrit = false;
  if (isHit) {
    const critChance =
      (enemy.getCriticalPoints(state) *
        (options?.critChanceMultiplier ?? 1.0)) /
      100.0;
    isCrit = Math.random() < (critChance ?? 0);

    if (isCrit) {
      const critResistance = applyTransformations(
        critResistanceByTargetTags(target),
        state,
        0,
        { target, tags: options?.tags },
      );
      if (Math.random() < critResistance) {
        isCrit = false;
      }
    }

    const critMultiplier = 1.5;
    damage = isCrit ? attack * critMultiplier : attack;
    damage = applyTransformations(
      attackDamageReceivedByTargetTags(target),
      state,
      damage,
      {
        target,
        tags: options?.tags,
      },
    );
    state = modifyTargetCurrentHP(state, target, -damage, options?.cause);
  }
  const enemyAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Enemy,
    target,
    tags: options?.tags ?? [],
  };
  state = pushCombatActionResult(state, enemyAction);
  return state;
}

export function drainingEnemyAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    critChanceMultiplier?: number;
    forcedTarget?: AttackTarget;
    cause?: string;
    tags?: string[];
  },
): GameState {
  const enemy = getCurrentEnemy(state);
  const modifiedAccuracyPoints =
    enemy.getAccuracyPoints(state) * (options?.accuracyMultiplier ?? 1.0);
  const target = options?.forcedTarget ?? calculateAttackTarget(state);
  const isHit = calculateIsEnemyHit(state, modifiedAccuracyPoints, target);
  let damage = 0,
    isCrit = false;
  if (isHit) {
    const critChance =
      (enemy.getCriticalPoints(state) *
        (options?.critChanceMultiplier ?? 1.0)) /
      100.0;
    isCrit = Math.random() < (critChance ?? 0);

    if (isCrit) {
      const critResistance = applyTransformations(
        critResistanceByTargetTags(target),
        state,
        0,
        { target, tags: options?.tags },
      );
      if (Math.random() < critResistance) {
        isCrit = false;
      }
    }

    const critMultiplier = 1.5;
    damage = isCrit ? attack * critMultiplier : attack;
    const defense = getStatFromTarget(state, target, CombatStat.Defense);
    damage = calculateDamage(
      isCrit ? attack * critMultiplier : attack,
      isCrit ? 0 : defense,
    );
    damage = applyTransformations(
      attackDamageReceivedByTargetTags(target),
      state,
      damage,
      {
        target,
        tags: options?.tags,
      },
    );
    state = modifyTargetCurrentHP(state, target, -damage, options?.cause);
    state = modifyEnemyCurrentHP(state, +damage);

    const healingAction = {
      id: Math.random().toString(),
      damage: -damage,
      isHit: true,
      isCrit: false,
      time: getSecondsPlayed(state),
      source: AttackTarget.Enemy,
      target: AttackTarget.Enemy,
      tags: options?.tags ?? [],
    };
    state = pushCombatActionResult(state, healingAction);
  }

  const enemyAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Enemy,
    target: AttackTarget.Player,
    tags: options?.tags ?? [],
  };
  state = pushCombatActionResult(state, enemyAction);

  return state;
}

export function dispelAllEnemyAttackEffect(
  state: GameState,
  includeBeneficial?: boolean,
): GameState {
  const tempEffects = getTemporaryEffects(state);
  tempEffects.forEach((tempEffect) => {
    const tempEffectObj = TemporaryEffects.getById(tempEffect.id);
    if (
      tempEffect.target == AttackTarget.Enemy &&
      tempEffectObj.isDispellable() &&
      (includeBeneficial || !tempEffectObj.isBeneficial())
    ) {
      state = clearTemporaryEffect(tempEffect.id, AttackTarget.Enemy)(state);
    }
  });
  return state;
}

export function standardFamiliarAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    critChanceMultiplier?: number;
    forcedTarget?: AttackTarget;
    cause?: string;
    tags?: string[];
    onHit?: (state: GameState, actionResult: CombatActionResult) => GameState;
  },
): GameState {
  const target = options?.forcedTarget ?? AttackTarget.Enemy;
  const familiar = getCurrentCompanionClass(state);
  const dodgePoints = getStatFromTarget(state, target, CombatStat.Dodge);
  const familiarStats = familiar.getStatsForCompanion(state);
  const modifiedAccuracyPoints =
    familiarStats.accuracy * (options?.accuracyMultiplier ?? 1.0);
  const isHit = calculateIsHit(modifiedAccuracyPoints, dodgePoints);
  let damage = 0,
    isCrit = false;
  if (isHit) {
    const critChance =
      (familiarStats.crit * (options?.critChanceMultiplier ?? 1.0)) / 100.0;
    isCrit = Math.random() < (critChance ?? 0);
    const critResistance = applyTransformations(
      critResistanceByTargetTags(target),
      state,
      0,
      { target, tags: options?.tags },
    );
    if (Math.random() < critResistance) {
      isCrit = false;
    }
    const critMultiplier = 1.5;

    damage = calculateDamage(
      isCrit ? attack * critMultiplier : attack,
      isCrit ? 0 : getStatFromTarget(state, target, CombatStat.Defense),
    );
    damage = applyTransformations(
      attackDamageReceivedByTargetTags(target),
      state,
      damage,
      {
        target,
        source: AttackTarget.Familiar,
        tags: options?.tags,
      },
    );
    state = modifyTargetCurrentHP(state, target, -damage, options?.cause);
  }
  const summonAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Familiar,
    target: target,
    tags: options?.tags ?? [],
  };
  state = pushCombatActionResult(state, summonAction);
  state = options?.onHit != null ? options.onHit(state, summonAction) : state;
  return state;
}

export function drainingSummonAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    critChanceMultiplier?: number;
    forcedTarget?: AttackTarget;
    cause?: string;
    tags?: string[];
  },
): GameState {
  const familiar = getCurrentCompanionClass(state);
  const target = options?.forcedTarget ?? AttackTarget.Enemy;
  const dodgePoints = getStatFromTarget(state, target, CombatStat.Dodge);
  const familiarStats = familiar.getStatsForCompanion(state);
  const modifiedAccuracyPoints =
    familiarStats.accuracy * (options?.accuracyMultiplier ?? 1.0);
  const isHit = calculateIsHit(modifiedAccuracyPoints, dodgePoints);
  let damage = 0,
    isCrit = false;
  if (isHit) {
    const critChance =
      familiarStats.crit * (options?.critChanceMultiplier ?? 1.0);
    isCrit = Math.random() < (critChance ?? 0);
    const critResistance = applyTransformations(
      critResistanceByTargetTags(target),
      state,
      0,
      { target, tags: options?.tags },
    );
    if (Math.random() < critResistance) {
      isCrit = false;
    }
    const critMultiplier = 1.5;

    damage = calculateDamage(
      isCrit ? attack * critMultiplier : attack,
      isCrit ? 0 : getStatFromTarget(state, target, CombatStat.Defense),
    );
    damage = applyTransformations(
      attackDamageReceivedByTargetTags(target),
      state,
      damage,
      {
        target: AttackTarget.Enemy,
        source: AttackTarget.Familiar,
        tags: options?.tags,
      },
    );
    state = modifyTargetCurrentHP(state, target, -damage, options?.cause);
    state = modifyCompanionCurrentHP(state, +damage, options?.cause);
    const healingAction = {
      id: Math.random().toString(),
      damage: -damage,
      isHit,
      isCrit: false,
      time: getSecondsPlayed(state),
      source: AttackTarget.Familiar,
      target: AttackTarget.Familiar,
      tags: options?.tags ?? [],
    };
    state = pushCombatActionResult(state, healingAction);
  }
  const summonAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Familiar,
    target: target,
    tags: options?.tags ?? [],
  };
  state = pushCombatActionResult(state, summonAction);
  return state;
}

export function standardPlayerAttackEffect(
  state: GameState,
  attack: number,
  options?: {
    accuracyMultiplier?: number;
    overrideAccuracy?: number;
    critChanceMultiplier?: number;
    critChanceAddition?: number;
    targetDefenseMultiplier?: number;
    overrideCritChance?: number;
    forcedTarget?: AttackTarget;
    cause?: string;
    tags: string[];
  },
): GameState {
  const target = options?.forcedTarget ?? AttackTarget.Enemy;
  const accuracyPoints =
    options?.overrideAccuracy ??
    calculatePlayerAccuracyPoints(state) * (options?.accuracyMultiplier ?? 1.0);
  const dodgePoints = getStatFromTarget(state, target, CombatStat.Dodge);
  const isHit = calculateIsHit(accuracyPoints, dodgePoints);
  let damage = 0,
    isCrit = false;
  if (isHit) {
    const critChancePoints =
      options.overrideCritChance ??
      calculatePlayerCritPoints(state) + (options?.critChanceAddition ?? 0);
    isCrit =
      Math.random() <
      (critChancePoints * (options?.critChanceMultiplier ?? 1.0)) / 100;
    const critResistance = applyTransformations(
      critResistanceByTargetTags(target),
      state,
      0,
      { target, tags: options?.tags },
    );
    if (Math.random() < critResistance) {
      isCrit = false;
    }
    const critMultiplier = 1.5;
    damage = calculateDamage(
      isCrit ? attack * critMultiplier : attack,
      isCrit
        ? 0
        : getStatFromTarget(state, target, CombatStat.Defense) *
            (options?.targetDefenseMultiplier ?? 1.0),
    );
    damage = applyTransformations(
      attackDamageReceivedByTargetTags(target),
      state,
      damage,
      {
        target,
        source: AttackTarget.Player,
        tags: options?.tags,
      },
    );
    state = modifyTargetCurrentHP(state, target, -damage, options?.cause);
  }
  const playerAction = {
    id: Math.random().toString(),
    damage,
    isHit,
    isCrit,
    time: getSecondsPlayed(state),
    source: AttackTarget.Player,
    target: target,
    tags: options?.tags ?? [],
  };
  state = pushCombatActionResult(state, playerAction);

  return state;
}

export function getCurrentEnemyAction(state: GameState) {
  return lastSelectedEnemyAction;
}

export function getCurrentFamiliarAction(state: GameState) {
  return lastSelectedFamiliarAction;
}

export function getMessageLog(state: GameState) {
  return state.run.explorationMessageLog || [];
}

export function getCumulativeLosses(state: GameState, enemyId: string): number {
  return state.world.timesEnemyDefeated?.[enemyId]?.losses ?? 0;
}

export function incrementCumulativeLosses(
  state: GameState,
  enemyId: string,
): GameState {
  const timesEnemyDefeated = state.world.timesEnemyDefeated?.[enemyId] ?? {
    player: 0,
    familiars: 0,
    losses: 0,
  };

  timesEnemyDefeated.losses += 1;

  state.world.timesEnemyDefeated[enemyId] = timesEnemyDefeated;

  return state;
}

export function getSuccessfulExplorationsForFloor(
  state: GameState,
  dungeonFloorId: string,
) {
  return (
    state.run.exploration?.successfulExplorationsPerFloor?.[dungeonFloorId] || 0
  );
}

export function pushToMessageLog(state: GameState, log: string): GameState {
  if (!state.run.explorationMessageLog) {
    state.run.explorationMessageLog = [];
  }
  state.run.explorationMessageLog.unshift({
    id: Math.random().toString(),
    realTimestamp: Date.now(),
    secondsPlayed: getSecondsPlayed(state),
    message: log,
    accessibleMessage: log,
  });
  state.run.explorationMessageLog = state.run.explorationMessageLog.slice(
    0,
    40,
  );
  return state;
}

export function endExploration(state: GameState): GameState {
  if (!state.run.exploration) {
    state.run.exploration = EXPLORATION_INITIAL_STATE;
  }
  state.run.exploration.explorationStatus = ExplorationStatus.None;
  state = pushToMessageLog(state, `You stop exploring.`);
  return state;
}

registerTransformation(
  [[TransformationTags.ExplorationDelay]],
  "PauseExplorationHandler",
  t("Pause Exploration"),
  TransformationType.Override,
  (state) => (hasFlag(state, "exploration_paused") ? Infinity : undefined),
);

export function pauseExploration(state: GameState): GameState {
  state = setFlag("exploration_paused")(state);
  return state;
}

export function resumeExploration(state: GameState): GameState {
  state = clearFlag("exploration_paused")(state);
  state.run.exploration.explorationStartTime = getSecondsPlayed(state);
  return state;
}

const explorationDelayTags = [TransformationTags.ExplorationDelay];
export function getExplorationTimeRequirementSec(state: GameState): number {
  const explorationStatus = getExplorationStatus(state);
  if (
    explorationStatus != ExplorationStatus.Exploring &&
    explorationStatus != ExplorationStatus.ExploringBoss
  ) {
    return 0;
  }
  const dungeonFloor = getCurrentDungeonFloor(state);
  return applyTransformations(
    explorationDelayTags,
    state,
    dungeonFloor.getExplorationRequiredTimeSecBase(),
  );
}

const familiarAttackDelayTags = [AttackTarget.Familiar, CombatStat.AttackDelay];
export function getFamiliarAttackDelay(state: GameState): number | undefined {
  const currentFamiliar = getCurrentCompanionClass(state);
  if (
    !currentFamiliar ||
    state.run.exploration?.explorationStatus != ExplorationStatus.Combat
  ) {
    return undefined;
  }

  if (!lastSelectedFamiliarAction) {
    lastSelectedFamiliarAction =
      currentFamiliar.getNextActionForCompanion(state);
  }

  // TODO: This is a bug, because the delay was calculated at the time the action
  // was generated, but now it was changed (i.e. stun or something).
  // If we transform it again, the effects like haste are doubled.
  // Need to refactor the familiars.
  return lastSelectedFamiliarAction.delay;
  // return applyTransformations(
  //   familiarAttackDelayTags,
  //   state,
  //   lastSelectedFamiliarAction.delay,
  // );
}

const enemyAttackDelayTags = [AttackTarget.Enemy, CombatStat.AttackDelay];
export function getEnemyAttackDelay(state: GameState): number {
  const currentEnemy = getCurrentEnemy(state);
  if (!lastSelectedEnemyAction) {
    lastSelectedEnemyAction = currentEnemy.getNextAction(state);
  }

  const baseEnemyAttackDelay = currentEnemy.getAttackDelay(state);
  const actualEnemyAttackDelay = getCachedCombatStat(
    state,
    AttackTarget.Enemy,
    CombatStat.AttackDelay,
    (state) =>
      applyTransformations(enemyAttackDelayTags, state, baseEnemyAttackDelay),
  );

  // actionAttackDelay is calculated based on base attack delay
  const actionAttackDelay = lastSelectedEnemyAction.delay;

  // So we convert
  return (actionAttackDelay * actualEnemyAttackDelay) / baseEnemyAttackDelay;
}

export function resetEnemyNextAction(): void {
  lastSelectedEnemyAction = undefined;
}

export function resetFamiliarNextAction(): void {
  lastSelectedFamiliarAction = undefined;
}

export function getEnemyTurnCounter(state: GameState): number {
  return getCombatVariable(state, "enemyTurnCounter") ?? 1;
}

export type EnemyDeathListener = (state: GameState, enemy: Enemy) => GameState;

const enemyDeathListeners: DataStore<EnemyDeathListener> = new DataStore();

export function addEnemyDeathListener(
  id: string,
  listener: EnemyDeathListener,
) {
  enemyDeathListeners.register(listener, id);
}

export function triggerEnemyDeathListeners(
  state: GameState,
  enemy: Enemy,
): GameState {
  enemyDeathListeners.getAll().forEach((listener) => {
    state = listener(state, enemy);
  });
  return state;
}

export function triggerCombatLoss(state: GameState): GameState {
  const currentEnemy = getCurrentEnemy(state);
  state = clearCombatTemporaryEffects(state);
  state = resetAllCombatVariables(state);
  if (currentEnemy.isBoss()) {
    state.run.autoAttemptBoss = false;
  }
  lastSelectedFamiliarAction = undefined;
  state.run.exploration.explorationStatus = ExplorationStatus.Reviving;
  if (hasCurrentActiveCompanion(state)) {
    getCurrentCompanionData(state).status.companionStatus =
      FamiliarCompanionStatusType.Preparing;
  }
  const currentEnemyName = currentEnemy?.getName();
  let fromEnemyMessage = "";
  if (currentEnemyName) {
    fromEnemyMessage = t(` from {{enemy}}`, {
      enemy: currentEnemyName,
    });
  }
  const cause =
    state.run.exploration.lastPlayerDamageCause ?? t("something unknown");
  state = pushToMessageLog(
    state,
    t(
      "You run away{{fromEnemyMessage}} to recover from your injuries caused by {{cause}}...",
      { fromEnemyMessage: fromEnemyMessage, cause: cause },
    ),
  );
  state = incrementAccumulatorVariable(state, "deathCount", 1);
  state = incrementAccumulatorVariable(
    state,
    "deathCountIn-" + getCurrentDungeonFloor(state).getId(),
    1,
  );
  if (currentEnemy != null) {
    incrementCumulativeLosses(state, currentEnemy.getId());
    state = currentEnemy.onPlayerDeath(state);
  }
  return state;
}

export function loadExploration() {
  registerTimeTickListener(
    "Exploration",
    (state: GameState, timeElapsedSec: number) => {
      const explorationStatus = getExplorationStatus(state);
      if (
        explorationStatus != ExplorationStatus.Exploring &&
        explorationStatus != ExplorationStatus.ExploringBoss
      ) {
        return state;
      }

      const dungeonFloor = getCurrentDungeonFloor(state);
      const explorationTimeRequirement =
        getExplorationTimeRequirementSec(state);

      const nextExplorationTime =
        getExplorationStartTime(state) + explorationTimeRequirement;
      if (getSecondsPlayed(state) > nextExplorationTime) {
        if (!state.run.exploration) {
          state.run.exploration = EXPLORATION_INITIAL_STATE;
        }
        state.run.exploration.explorationStartTime = nextExplorationTime;
        if (explorationStatus == ExplorationStatus.Exploring) {
          const outcome = dungeonFloor.doExplore(state, false);
          if (outcome != null) {
            return outcome.normal(state);
          }
        } else {
          const transform = dungeonFloor.doFightBoss(state);
          return transform ? transform(state) : state;
        }
      }

      return state;
    },
  );

  registerTimeTickListener(
    "Combat",
    (state: GameState, timeElapsedSec: number) => {
      const explorationStatus = getExplorationStatus(state);
      if (explorationStatus != ExplorationStatus.Combat) {
        return state;
      }

      const currentEnemy = getCurrentEnemy(state);
      const currentFamiliar = getCurrentCompanionClass(state);

      // First, pass the time
      const attackDelayPlayer = calculatePlayerAttackDelaySec(state);
      const attackDelayEnemy = getEnemyAttackDelay(state);
      const attackDelayFamiliar = getFamiliarAttackDelay(state);

      state.run.exploration.playerActionProgress +=
        timeElapsedSec / attackDelayPlayer;
      state.run.exploration.enemyActionProgress +=
        timeElapsedSec / attackDelayEnemy;
      if (attackDelayFamiliar != null && hasCurrentActiveCompanion(state)) {
        state.run.exploration.familiarActionProgress +=
          timeElapsedSec / attackDelayFamiliar;
      }

      while (state.run.exploration.playerActionProgress > 1.0) {
        state.run.exploration.playerActionProgress -= 1.0;
        let extraSec =
          state.run.exploration.playerActionProgress * attackDelayPlayer;
        if (extraSec == Infinity) {
          // Guard against stunning
          extraSec = 0;
        }
        state = executePlayerAutoAttack(state);
        const newAttackDelay = calculatePlayerAttackDelaySec(state);
        state.run.exploration.playerActionProgress = extraSec / newAttackDelay;
      }

      if (
        lastSelectedFamiliarAction != null &&
        attackDelayFamiliar != null &&
        state.run.exploration.familiarActionProgress > 1.0
      ) {
        state.run.exploration.familiarActionProgress -= 1.0;
        const extraSec =
          state.run.exploration.familiarActionProgress * attackDelayFamiliar;
        state = executeFamiliarAction(state);
        lastSelectedFamiliarAction =
          currentFamiliar?.getNextActionForCompanion(state);
        const newAttackDelay = getFamiliarAttackDelay(state);
        if (newAttackDelay != null) {
          state.run.exploration.familiarActionProgress =
            extraSec / newAttackDelay;
        }
      }

      if (state.run.exploration.enemyActionProgress > 1.0) {
        state.run.exploration.enemyActionProgress -= 1.0;
        let extraSec =
          state.run.exploration.enemyActionProgress * attackDelayEnemy;
        if (extraSec == Infinity) {
          // Guard against stunning
          extraSec = 0;
        }
        state = executeEnemyAction(state);
        state = setCombatVariable(
          "enemyTurnCounter",
          getEnemyTurnCounter(state) + 1,
        )(state);
        lastSelectedEnemyAction = currentEnemy.getNextAction(state);
        const newAttackDelay = getEnemyAttackDelay(state);
        state.run.exploration.enemyActionProgress = extraSec / newAttackDelay;
      }

      return state;
    },
  );

  addGameStateListener(
    "CombatFamiliarDeathListener",
    (newState: GameState) => {
      if (getExplorationStatus(newState) != ExplorationStatus.Combat) {
        return;
      }

      if (
        hasCurrentActiveCompanion(newState) &&
        (getCurrentFamiliarHP(newState) <= 0 ||
          getCurrentCompanionData(newState).status.triggerFamiliarDeath)
      ) {
        return (state) => {
          lastSelectedFamiliarAction = undefined;
          state.run.exploration.familiarActionProgress = 0;

          const familiar = getCurrentCompanionData(state);
          if (familiar == null) {
            return state;
          }

          state = pushToMessageLog(
            state,
            t("Your familiar is exhausted and needs to recover!"),
          );
          familiar.status.triggerFamiliarDeath = false;
          familiar.status.companionStatus =
            FamiliarCompanionStatusType.Reviving;
          state = clearCombatStatCache(state, AttackTarget.Familiar);
          return state;
        };
      }
    },
    (state) => getCurrentFamiliarHP(state),
  );

  addGameStateListener(
    "CombatCleaningCrew",
    (newState: GameState) => {
      if (getExplorationStatus(newState) !== ExplorationStatus.Combat) {
        return (state) => {
          state = clearCombatTemporaryEffects(state);
          state = resetAllCombatVariables(state);
          for (let itemOccurrence of getEquippedItems(
            state,
            EquipmentSlot.Pouch,
          )) {
            if (itemOccurrence != null) {
              const item = Items.getById(itemOccurrence.itemId) as PouchItem;
              state = item.doCleanup(state, itemOccurrence.params);
            }
          }
          state.run.exploration.pouchItemsUsesLeft = {};
          state.run.exploration.triggerPlayerDeath = false;
          lastSelectedFamiliarAction = undefined;
          return state;
        };
      }
    },
    (state) => getExplorationStatus(state),
  );

  addHighPriorityGameStateListener(
    "CombatDeathListener",
    (newState: GameState) => {
      if (getExplorationStatus(newState) != ExplorationStatus.Combat) {
        return;
      }

      if (
        getCurrentPlayerHP(newState) <= 0 ||
        newState.run.exploration.triggerPlayerDeath
      ) {
        newState.run.exploration.triggerPlayerDeath = false;
        const shouldPreventDeath = DeathInterceptors.getAll()
          .map((interceptor) => interceptor(newState, AttackTarget.Player))
          .some((value) => value);
        if (!shouldPreventDeath) {
          // Player death occurred
          return triggerCombatLoss;
        }
      }

      if (getCurrentEnemyHP(newState) <= 0) {
        const shouldPreventDeath = DeathInterceptors.getAll()
          .map((interceptor) => interceptor(newState, AttackTarget.Enemy))
          .some((value) => value);
        if (!shouldPreventDeath) {
          return (state) => {
            const currentEnemy = getCurrentEnemy(state);
            state = pushToMessageLog(
              state,
              t("You defeated {{enemy}}!", { enemy: currentEnemy.getName() }),
            );
            state = currentEnemy.onDeath(state, false);
            const floorId = state.run.exploration.currentDungeonFloorId;
            const floor = DungeonFloors.getById(floorId);
            const couldFightBoss = floor.canFightBoss(state);
            state.run.exploration.successfulExplorationsPerFloor[floorId] =
              getSuccessfulExplorationsForFloor(state, floorId) + 1;
            const canFightBoss = floor.canFightBoss(state);
            if (couldFightBoss != canFightBoss) {
              state = clearCacheKey(
                state,
                (_state) => "floorsAvailableForFamiliars",
              );
            }
            state = triggerEnemyDeathListeners(state, currentEnemy);

            if (state.options.waitUntilFullyHealedBeforeExplore) {
              state.run.exploration.explorationStatus =
                ExplorationStatus.Reviving;
              return state;
            }

            return startExplorationOrAutoAdvance(state);
          };
        }
      }
    },
  );

  registerTimeTickListener(
    "PlayerHPRegen",
    (state: GameState, timeElapsedSec: number) => {
      const explorationStatus = getExplorationStatus(state);

      // This is necessary or else the player never dies because they regenerate HP
      // in the same update
      const currentHP = getCurrentPlayerHP(state);
      if (explorationStatus == ExplorationStatus.Combat && currentHP <= 0) {
        return state;
      }

      const playerMaxHP = calculatePlayerMaxHP(state);
      if (currentHP >= playerMaxHP) {
        return state;
      }

      const hpRegenBase = calculatePlayerHPRegenRatioPerSec(state);
      let hpRegenMultiplier = 1.0;
      if (explorationStatus != ExplorationStatus.Combat) {
        hpRegenMultiplier = 10.0;
      }
      state = modifyPlayerCurrentHP(
        state,
        playerMaxHP * hpRegenBase * hpRegenMultiplier * timeElapsedSec,
        t("HP Regeneration"),
      );
      return state;
    },
  );

  function startExplorationOrAutoAdvance(state: GameState): GameState {
    if (
      state.run.autoAdvanceToNextFloor &&
      getCurrentDungeonFloor(state).getNextFloor(state)?.isUnlocked?.(state)
    ) {
      return startExplorationOnFloor(
        state,
        getCurrentDungeonFloor(state).getNextFloor(state),
      );
    } else if (
      state.run.autoAttemptBoss &&
      getCurrentDungeonFloor(state).canFightBoss(state)
    ) {
      return startBossExploration(state);
    }

    return startExploration(state);
  }

  addGameStateListener("ContinueExplorationAfterReviving", (newState) => {
    if (
      getExplorationStatus(newState) == ExplorationStatus.Reviving &&
      getCurrentPlayerHP(newState) >= calculatePlayerMaxHP(newState) * 0.99
    ) {
      return startExplorationOrAutoAdvance;
    }
  });
}
