import React, { useMemo } from "react";
import { Platform, StyleSheet, View } from "react-native";
import LibraryMarkdown, {
  MarkdownIt,
  getUniqueID,
} from "react-native-markdown-display";
import { useTheme } from "react-native-paper";
import { Text } from "../utility/Text";
import type { Props as PaperTooltipProps } from "../utility/tooltip/PaperTooltip";
import { GameIcon } from "./icons/GameIcon";
import MarkdownIconPlugin from "./markdown/MarkdownIconPlugin";
import MarkdownTooltipPlugin from "./markdown/MarkdownTooltipPlugin";

let Tooltip: React.ComponentType<PaperTooltipProps> = null;

export function setTooltipComponentForMarkdown(
  component: React.ComponentType<PaperTooltipProps>,
) {
  Tooltip = component;
}

const markdownItInstance = MarkdownIt({ typographer: false })
  .use(MarkdownIconPlugin, {
    containerClassName: "icon-embed",
  })
  .use(MarkdownTooltipPlugin, {});

const markdownRules = {
  paragraph: (node: any, children: any, parent: any, markdownStyles: any) => {
    return (
      <View key={node.key} style={[node.index > 0 && styles.markdownParagraph]}>
        {children}
      </View>
    );
  },
  text: (node, children, parent, styles, inheritedStyles = {}) => (
    <Text key={node.key} style={[inheritedStyles, styles.text]}>
      {node.content}
    </Text>
  ),
  "regexp-0": (node: any, children: any, parent: any, markdownStyles: any) => {
    const icon = node.sourceMeta.match[1];
    // \u200b is "zero-width space". It needs to be added because
    // in devices like the Google Pixel phones and tablets,
    // if a line contains exclusively the icon with no text,
    // the icon gets cut off.
    return Platform.select({
      default: (
        <Text key={getUniqueID()}>
          <GameIcon
            key={getUniqueID()}
            style={
              icon === "attack"
                ? styles.imageStyleAndroidAttack
                : styles.imageStyleAndroid
            }
            small={true}
            resizeMode="contain"
            icon={icon}
          />
          {Platform.OS === "android" && "\u200b"}
        </Text>
      ),
      ios: (
        <Text key={getUniqueID()}>
          <View
            style={
              icon === "attack"
                ? styles.imageContainerStyleiOSAttack
                : styles.imageContainerStyleiOS
            }
          >
            <GameIcon
              key={getUniqueID()}
              style={
                icon === "attack"
                  ? styles.imageStyleiOSAttack
                  : styles.imageStyleiOS
              }
              small={true}
              resizeMode="contain"
              icon={icon}
            />
          </View>
        </Text>
      ),
    });
  },
  "regexp-1": (
    node: any,
    children: any,
    parent: any,
    markdownStyles: any,
    inheritedStyles: any,
  ) => {
    const base = node.sourceMeta.match[1];
    const tooltip = node.sourceMeta.match[2];
    if (tooltip == "") {
      return (
        <Text
          key={node.key}
          style={[inheritedStyles, markdownStyles.text, styles.text]}
        >
          {base}
        </Text>
      );
    }
    return (
      <View key={getUniqueID()} style={styles.tooltipTextContainer}>
        <Tooltip title={tooltip}>
          <Text
            style={[
              inheritedStyles,
              markdownStyles.text,
              styles.text,
              styles.underlined,
            ]}
          >
            {base}
          </Text>
        </Tooltip>
      </View>
    );
  },
};

function MarkdownImpl(props: any) {
  const style = props?.style || {};
  const containerStyle = props?.containerStyle || {};
  const children = props.children?.replace?.(
    /((\d(K|M|B|T)?)|(Max)) :/g,
    (_substring: string, args: string) => args + ":",
  );
  const accessibilityLabel =
    props.accessibilityLabel ??
    (props.children as string)
      ?.replaceAll?.("*", "")
      ?.replaceAll(/\^(.+?)\^<(.*?)>/g, (substring, ...matches) => matches[0]);
  const accessibilityLiveRegion = props?.accessibilityLiveRegion ?? "none";
  const theme = useTheme();
  const fullStyle = useMemo(
    () => ({
      body: [
        {
          justifyContent: "flex-end",
          minHeight: 21,
          lineHeight: Platform.select({ ios: 19, default: undefined }),
          color: theme.colors.onBackground,
        },
        style,
      ],
      bullet_list_icon: {
        lineHeight: Platform.select({ ios: 20, default: 16 }),
        marginTop: 4,
      },
      bullet_list: { width: "100%" },
      list_item: { alignItems: "center", justifyContent: "center" },
      heading2: {
        lineHeight: 24,
      },
    }),
    [theme, style],
  );
  return useMemo(
    () => (
      <View
        accessibilityLabel={accessibilityLabel}
        accessibilityLiveRegion={accessibilityLiveRegion}
        accessible={true}
        style={[styles.containerView, containerStyle]}
      >
        <LibraryMarkdown
          {...props}
          markdownit={markdownItInstance}
          rules={markdownRules}
          style={fullStyle}
        >
          {children}
        </LibraryMarkdown>
      </View>
    ),
    [
      accessibilityLabel,
      accessibilityLiveRegion,
      markdownItInstance,
      markdownRules,
      fullStyle,
      children,
    ],
  );
}

const Markdown = React.memo(MarkdownImpl);

export { Markdown };

const styles = StyleSheet.create({
  markdownParagraph: {
    marginTop: Platform.select({ ios: 8, default: 4 }),
  },
  markdownTextgroup: {
    flexDirection: "row",
    flexWrap: "wrap",
    alignItems: "center",
    padding: 0,
    margin: 0,
  },
  text: {
    lineHeight: 16,
  },
  imageStyleAndroid: {
    height: 16,
    width: 16,
    justifyContent: "flex-end",
    alignItems: "center",
    top: 2,
    marginLeft: 2,
  },
  imageStyleAndroidAttack: {
    height: 16,
    width: 16,
    justifyContent: "flex-end",
    alignItems: "center",
    top: 2,
    marginLeft: 2,
    marginRight: 2,
  },
  imageContainerStyleiOS: {
    width: 18,
    alignItems: "center",
    justifyContent: "center",
  },
  imageContainerStyleiOSAttack: {
    width: 20,
    alignItems: "center",
    justifyContent: "center",
  },
  imageStyleiOS: {
    height: 16,
    width: 16,
    justifyContent: "center",
    alignItems: "center",
    alignSelf: "center",
    top: 2,
  },
  imageStyleiOSAttack: {
    height: 16,
    width: 16,
    justifyContent: "center",
    alignItems: "center",
    alignSelf: "center",
    top: 2,
    right: 2,
  },
  containerView: Platform.select({
    default: {},
    web: {
      flex: 1,
    },
  }),
  underlined: {
    textDecorationLine: "underline",
    top: 2,
  },
  tooltipTextContainer: {
    height: 16,
    // justifyContent: "flex-end",
    // lineHeight: 16,
  },
});
