import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import expandHome from "../../action/actions/ExpandHome";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { Storylines } from "../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";

class LandTreaty extends Enhancement {
  getId(): string {
    return "landTreaty";
  }
  getBaseName(): string {
    return t("Land Treaty");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_LAND;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 900,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 7,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Make a deal with the town to be able to purchase land for cheaper.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("Reduces the cost to expand your home");
  }
  isVisible(state: GameState): boolean {
    return (
      !Storylines.getByIdNullable("blessedLand")?.isBonusActive?.(state) &&
      super.isVisible(state)
    );
  }
}

const landTreaty = new LandTreaty();

registerTransformation(
  [[expandHome.getId(), TransformationTags.CostScale]],
  landTreaty.getId(),
  landTreaty.getBaseName(),
  TransformationType.Multiplier,
  (state) => (landTreaty.isPurchased(state) ? 0.7 : 1.0),
);

export { landTreaty };
