import { t } from "../../../../utils/I18n";
import { essenceStorage } from "../../../buildings/storages/EssenceStorage";
import { sewers } from "../../../exploration/dungeons/sewers/Sewers";
import { getCurrentDungeonFloor } from "../../../exploration/Exploration";
import { ExplorationStatus } from "../../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../../exploration/ExplorationUtils";
import {
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../../../items/Inventory";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { getElementLevel, SpellElement } from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { registerRandomEventTrigger } from "../../autorandom/RandomEventTrigger";
import { buildEvent } from "../../EventMessageBuilder";
import { EventTag } from "../../GameEvent";

const passageOfStenchStoryline = new Storyline(
  "passageOfStench",
  t("Passage of Stench"),
  260,
  20,
  (state) => t("Explore the Sewers"),
  t("+100% effectivity of Essence Storages"),
  (_state) => true,
);

export function loadPassageOfStench() {
  Storylines.register(passageOfStenchStoryline);

  const passageOfStenchEventBuilder = buildEvent(
    "passageOfStench",
    t("(Storyline) Passage of Stench"),
    [EventTag.STORYLINE],
  ).setStorylineIds([passageOfStenchStoryline.getId()]);

  passageOfStenchEventBuilder.message(
    t(`A sharp, terrible odor pierces your nostrils. You stop, feeling quite weakened by it. Your hired wizard also feels similarly.
    
"Agh... should we turn around? Why are we even doing this...? Is this Philosopher's Stone really that important...?" she asks, while complaining.

This time, you agree with her. This stench feels dangerously strong. You will have to turn around...`),
  );
  passageOfStenchEventBuilder
    .message(
      t(
        `Unless... maybe there is a way? You remind yourself you are a Wizard, after all... shouldn't you be able to use magic to solve something like this?`,
      ),
    )
    .option(
      t(
        "Transmute two protective masks (Requires Lv15 Earth, Lv15 Water, 16 Cotton, 2400 Mana)",
      ),
      {
        transform: (state) => {
          state = removeFromInventoryWithAnyParams(cotton, 16, state);
          state = grantResource(Resource.Mana, -2400)(state);
          return state;
        },
        isEnabled: (state) => {
          return (
            getResourceAmount(state, Resource.Mana) >= 2400 &&
            getTotalAmountOfItem(cotton, state) >= 16 &&
            getElementLevel(state, SpellElement.Earth) >= 15 &&
            getElementLevel(state, SpellElement.Water) >= 15
          );
        },
      },
      "masks",
    )
    .option(
      t("Dispel the stench with wind (Requires Lv18 Air, 3200 Mana)"),
      {
        transform: (state) => {
          state = grantResource(Resource.Mana, -3200)(state);
          return state;
        },
        isEnabled: (state) => {
          return (
            getResourceAmount(state, Resource.Mana) >= 3200 &&
            getElementLevel(state, SpellElement.Air) >= 18
          );
        },
      },
      "wind",
    )
    .option(t("Maybe later"))
    .end();

  passageOfStenchEventBuilder
    .message(
      t(
        `You transmute a pair of face masks, and give one to your hired wizard. Putting them on is a relief. You still don't feel fully well, but you get some of your strength back, and feel strong enough to press on.`,
      ),
    )
    .tag("masks")
    .option(t("Continue"), "past")
    .end();

  passageOfStenchEventBuilder
    .message(
      t(`You gather Mana and cast a strong Air spell. A strong breeze comes from behind you. Your hired wizard almost loses her hat, but manages to catch it at the last minute. She is about to complain, but she notices the same that you have - the air around you is more breathable.
    
Although not exactly pleasant, it feels like it isn't overpowering enough to stop you from advancing in this direction now...`),
    )
    .tag("wind");

  passageOfStenchEventBuilder
    .message(
      t(`You continue walking through the passage and eventually find the source of the foul smell: a big, big pile of what looks like rotten Goblin flesh. Your hired wizard is very disgusted by the sight and decides to wait for you outside. But you decide to scan the place.
      
Although you don't find much, one thing catches your attention: a book in the center that seems wildly unrelated to the room, *"Essence Storage Construction"*. You decide to take it with you - it is in good condition, and whoever put all those... Goblins... in here, for some reason, also decided to keep this book as well? You can't make much sense of it.`),
    )
    .tag("past")
    .option(
      t("Continue"),
      { transform: (state) => passageOfStenchStoryline.complete(state) },
      "end",
    );

  // Scribbled book describing how to store Corpses diagram,

  passageOfStenchEventBuilder
    .message(
      t(`In the comfort of your home, you open the book and decide to take a look at it. You discover two different things. The first one are guides on how to better build Essence Storages, but they seem somewhat difficult to understand. The second one are crude diagrams written by someone else about the best way to pile Goblin corpses... which you find odd. It appears someone has just used this valuable book as a writing material for another purpose!
      
You are somewhat disgusted, but you decide to store the book anyway. You think the knowledge about how to better build Essence Storages might be able to benefit someone else at some point...

**You have completed the "Passage of Stench" Storyline! In future runs, the knowledge from the book will double the effect of Essence Storages!**`),
    )
    .tag("end")
    .end();

  const passageOfStench = passageOfStenchEventBuilder.build();

  registerTransformation(
    [[TransformationTags.PerBuildingCap, essenceStorage.getId()]],
    "passageOfStench",
    passageOfStenchStoryline.getName(),
    TransformationType.Multiplier,
    (state) => (passageOfStenchStoryline.isBonusActive(state) ? 2.0 : 1.0),
  );

  registerRandomEventTrigger(
    passageOfStench,
    25,
    (state) =>
      !passageOfStench.hasTriggered(state) &&
      !passageOfStenchStoryline.isCompleted(state) &&
      getExplorationStatus(state) != ExplorationStatus.None &&
      sewers.getDungeonFloors().includes(getCurrentDungeonFloor(state)),
  );
}

export { passageOfStenchStoryline };
