import { Flag } from "../Flag";
import { hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { Challenge } from "./Challenge";

const Challenges = new DataStore<Challenge>();

export { Challenges };

export function getCurrentChallenge(state: GameState): Challenge | undefined {
  const currentChallengeId = state.run.currentChallengeId;
  if (!currentChallengeId) {
    return undefined;
  }
  return Challenges.getById(currentChallengeId);
}

export function getVisibleChallenges(state: GameState): Challenge[] {
  if (!areChallengesUnlocked(state)) {
    return [];
  }

  return Challenges.getAll().filter((challenge) => challenge.isVisible(state));
}

export function areChallengesUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.ChallengesUnlocked);
}
