import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { BuildWizardChamber } from "../../buildings/WizardChamber";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";

class ScalableChambers extends Enhancement {
  getId(): string {
    return "scalableChambers";
  }
  getBaseName(): string {
    return t("Scalable Chambers");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_OTHER;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 400,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 5,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Tweak the way you build Wizard Chambers, to make them more affordable.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("Reduces the cost to build Wizard Chambers");
  }
}

export const scalableChambers = new ScalableChambers();

registerTransformation(
  [[BuildWizardChamber.getId(), TransformationTags.CostScale]],
  scalableChambers.getId(),
  scalableChambers.getBaseName(),
  TransformationType.Multiplier,
  (state) => (scalableChambers.isPurchased(state) ? 0.7 : 1.0),
);
