import React, { useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import {
  EquipmentSlot,
  getEquippedExtraSlotItem,
  getEquippedItem,
  getMaxEquippedItemsPerSlot,
  getUnequipAction,
} from "../../backend/items/Equipment";
import { Items } from "../../backend/items/Items";
import { EquippableItem } from "../../backend/items/equipment/EquippableItem";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";
import { ItemInfo } from "./ItemInfo";
import { ItemTile } from "./ItemTile";

function EquippedItemSlot(props: {
  slot: EquipmentSlot;
  position?: number;
  onPressEquippedItem?: (slot: EquipmentSlot) => void;
}) {
  const state = useGameState();
  const slot = props.slot;
  const position = props.position;
  const equippedItem = getEquippedExtraSlotItem(state, slot, position);
  const actualEquippedItem = getEquippedItem(state, slot, position);
  const unequipAction = getUnequipAction(state, slot, position ?? 0);

  const stateRef = useRef(state);
  stateRef.current = state;

  const item = equippedItem && Items.getById(equippedItem.itemId);
  if (item && !(item instanceof EquippableItem)) {
    throw new Error("Only equippable items should be equipped");
  }

  const showAsDisabled = equippedItem != actualEquippedItem;
  return useMemo(() => {
    if (!equippedItem) {
      return (
        <View key={slot + position}>
          <ItemTile title={t("Empty")} />
          <Button
            compact={true}
            style={[styles.button, { height: 20 }]}
            labelStyle={{
              marginHorizontal: 0,
              flex: 1,
              marginVertical: 0,
              height: 20,
            }}
            contentStyle={{
              marginHorizontal: 0,
              height: 20,
            }}
            disabled={true}
            onPress={() =>
              unequipAction.transform &&
              stateRef.current.apply(unequipAction.transform)
            }
          >
            {unequipAction.description}
          </Button>
        </View>
      );
    }
    return (
      <View key={slot + position}>
        <ItemTile
          icon={(item as EquippableItem).getId()}
          quality={equippedItem?.params?.itemQuality}
          disabled={showAsDisabled}
          title={(item as EquippableItem).getName(equippedItem.params)}
          tooltip={
            <ItemInfo
              showTile={false}
              itemStack={{ itemOccurrence: equippedItem, amount: 1 }}
            />
          }
          tooltipIdSuffix={slot}
        />
        <Button
          compact={true}
          style={[styles.button, { height: 20 }]}
          labelStyle={{
            marginHorizontal: 0,
            flex: 1,
            marginVertical: 0,
            height: 20,
          }}
          contentStyle={{
            marginHorizontal: 0,
            height: 20,
          }}
          disabled={!unequipAction.isEnabled}
          onPress={() =>
            unequipAction.transform &&
            stateRef.current.apply(unequipAction.transform)
          }
        >
          {unequipAction.description}
        </Button>
      </View>
    );
  }, [
    JSON.stringify(equippedItem),
    showAsDisabled,
    slot,
    position,
    unequipAction.description,
    unequipAction.isEnabled,
    unequipAction.transform,
    stateRef,
    item?.getName(equippedItem?.params || {}),
  ]);
}

export default function EquippedItems(props: {
  onPressEquippedItem?: (slot: EquipmentSlot) => void;
}) {
  const state = useGameState();
  const slots = [
    EquipmentSlot.Hand,
    EquipmentSlot.Body,
    EquipmentSlot.Accessory,
  ];
  const slotComponents = slots.map((slot) => {
    const max = getMaxEquippedItemsPerSlot(state, slot);
    const components = [];
    for (let i = 0; i < max; i++) {
      components.push(
        <EquippedItemSlot
          key={slot + i}
          position={i}
          slot={slot as EquipmentSlot}
          onPressEquippedItem={props.onPressEquippedItem}
        />,
      );
    }
    return components;
  });

  return (
    <View style={Styles.flexRowAlignCenter}>
      <View>
        <Text h4>{t("Equip:")}</Text>
      </View>
      <View
        style={[
          Styles.ml16,
          Styles.flexRowAlignCenter,
          { flex: 1, gap: 4, flexWrap: "wrap" },
        ]}
      >
        {slotComponents.flat()}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  equipmentRow: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  row: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "flex-start",
    flex: 1,
  },
  labelStyle: {
    marginHorizontal: 8,
    flex: 1,
  },
  button: {
    height: 20,
  },
});
