import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { poison } from "./Poison";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Frail extends TemporaryEffect {
  getId(): string {
    return "frail";
  }
  getDisplayName(): string {
    return t("Frail");
  }
  getIcon() {
    return require("./frail.png");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("-10%:defense:; takes double damage from Poison effect");
  }
}

export const frail = new Frail();

export function loadFrail() {
  TemporaryEffects.register(frail);

  registerTransformation(
    [[poison.getId(), TransformationTags.PoisonDoTAmount]],
    frail.getId() + "poison",
    frail.getDisplayName(),
    TransformationType.Multiplier,
    (state, params) =>
      hasTemporaryEffect(state, frail.getId(), params.target) ? 2.0 : 1.0,
  );

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[target, CombatStat.Defense]],
      frail.getId() + "defense",
      frail.getDisplayName(),
      TransformationType.Multiplier,
      (state, params) =>
        hasTemporaryEffect(state, frail.getId(), target) ? 0.9 : 1.0,
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    frail.getId(),
    CombatStat.Defense,
  );
}
