import { valuesOfCached } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { ItemTagEnum } from "../items/ItemTagEnum";
import { SpellElement } from "../spells/Elements";

// Where can an action show up?
export enum ActionArea {
  HOME = "HOME",
  SPELL_MENU = "SPELL_MENU",
  STUDY = "STUDY",
  TRANSMUTE = "TRANSMUTE",
  EXPLORATION = "EXPLORATION",
  ENHANCEMENT = "ENHANCEMENT",
  BOOST = "BOOST",
  FAMILIARS = "FAMILIARS",
}

// From within an action area like Home, what are the subcategories an action should show up in?
export enum ActionSubcategories {
  // Home
  ACTIONS = "ACTIONS",
  CHANNELING = "CHANNELING",
  MANA = "MANA",
  STORAGE = "STORAGE",
  CATALYSTS = "CATALYSTS",
  OTHER = "OTHER",
  TIME_PIECES = "TIME_PIECES",

  // Enhance
  ENHANCE_STORAGE = "ENHANCE_STORAGE",
  ENHANCE_LAND = "ENHANCE_LAND",
  ENHANCE_PRODUCTION = "ENHANCE_PRODUCTION",
  ENHANCE_CATALYSTS = "ENHANCE_CATALYSTS",
  ENHANCE_EXPLORATION = "ENHANCE_COMBAT",
  ENHANCE_OTHER = "ENHANCE_OTHER",

  // Boost
  BOOST = "BOOST",
  BOOST_OTHER = "BOOST_OTHER",

  // Familiars
  FAMILIARS = "FAMILIARS",
}

export type ActionSubcategory =
  | ActionSubcategories
  | SpellElement
  | ItemTagEnum;

const HOME_ACTION_SUBCATEGORIES = [
  ActionSubcategories.ACTIONS,
  ActionSubcategories.CHANNELING,
  ActionSubcategories.MANA,
  ActionSubcategories.STORAGE,
  ActionSubcategories.CATALYSTS,
  ActionSubcategories.OTHER,
  ActionSubcategories.TIME_PIECES,
];

const STUDY_ACTION_SUBCATEGORIES = [ActionSubcategories.TIME_PIECES];

const ENHANCE_ACTION_SUBCATEGORIES = [
  ActionSubcategories.ENHANCE_STORAGE,
  ActionSubcategories.ENHANCE_LAND,
  ActionSubcategories.ENHANCE_PRODUCTION,
  ActionSubcategories.ENHANCE_CATALYSTS,
  ActionSubcategories.ENHANCE_EXPLORATION,
  ActionSubcategories.ENHANCE_OTHER,
];

const BOOST_ACTION_SUBCATEGORIES = [
  ActionSubcategories.BOOST,
  ActionSubcategories.BOOST_OTHER,
];

export function getSubcategoriesByArea(
  area: ActionArea,
): ActionSubcategories[] {
  switch (area) {
    case ActionArea.HOME:
      return HOME_ACTION_SUBCATEGORIES;
    case ActionArea.STUDY:
      return STUDY_ACTION_SUBCATEGORIES;
    case ActionArea.ENHANCEMENT:
      return ENHANCE_ACTION_SUBCATEGORIES;
    case ActionArea.BOOST:
      return BOOST_ACTION_SUBCATEGORIES;
    default:
      return valuesOfCached(ActionSubcategories);
  }
}

export function translateActionArea(area: ActionArea): string {
  switch (area) {
    case ActionArea.HOME:
      return t("Home");
    case ActionArea.SPELL_MENU:
      return t("Spells");
    case ActionArea.STUDY:
      return t("Study");
    case ActionArea.TRANSMUTE:
      return t("Transmute");
    case ActionArea.EXPLORATION:
      return t("Exploration");
    case ActionArea.ENHANCEMENT:
      return t("Enhance");
    case ActionArea.BOOST:
      return t("Boost");
    case ActionArea.FAMILIARS:
      return t("Familiars");
    // Do not add a default
  }
}

export function translateActionSubcategory(
  subcategory: ActionSubcategories,
): string {
  switch (subcategory) {
    case ActionSubcategories.ACTIONS:
      return t("Actions");
    case ActionSubcategories.CHANNELING:
      return t("Channeling");
    case ActionSubcategories.MANA:
      return t("Mana");
    case ActionSubcategories.STORAGE:
      return t("Storage");
    case ActionSubcategories.CATALYSTS:
      return t("Catalysts");
    case ActionSubcategories.OTHER:
      return t("Other");
    case ActionSubcategories.TIME_PIECES:
      return t("Time Pieces");

    case ActionSubcategories.ENHANCE_STORAGE:
      return t("Storage");
    case ActionSubcategories.ENHANCE_LAND:
      return t("Land");
    case ActionSubcategories.ENHANCE_PRODUCTION:
      return t("Production");
    case ActionSubcategories.ENHANCE_CATALYSTS:
      return t("Catalysts");
    case ActionSubcategories.ENHANCE_EXPLORATION:
      return t("Exploration");
    case ActionSubcategories.ENHANCE_OTHER:
      return t("Other");

    case ActionSubcategories.BOOST:
      return t("Boost");
    case ActionSubcategories.BOOST_OTHER:
      return t("Other");

    case ActionSubcategories.FAMILIARS:
      return t("Familiars");
    // Do not add a default
  }
}
