import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import { Button } from "./Button";
import { IconButton } from "./IconButton";

type PlusMinusAllocatorProps = {
  disableMinus: boolean;
  disablePlus: boolean;
  onPressMinus: () => void;
  onPressPlus: () => void;
};

export function PlusMinusAllocator(
  props: PlusMinusAllocatorProps = {
    disableMinus: false,
    disablePlus: false,
    onPressMinus: () => {},
    onPressPlus: () => {},
  },
) {
  const theme = useTheme();
  const minusButton = useMemo(
    () => (
      <Button
        enableAutoRepress={true}
        passthroughStyle={styles.plusMinusAllocatorButton}
        onPress={props.onPressMinus}
        disabled={props.disableMinus}
        compact={true}
        autoRepressConfig={{
          dropoffMultiplier: 0.8,
          startingThreshold: 300,
          startingRepressTime: 200,
          minTimeMs: 50,
        }}
        mode="contained"
        size={20}
        ButtonComponent={IconButton}
        icon="minus"
      ></Button>
    ),
    [props.onPressMinus, props.disableMinus],
  );
  const plusButton = useMemo(
    () => (
      <Button
        enableAutoRepress={true}
        passthroughStyle={styles.plusMinusAllocatorButton}
        onPress={props.onPressPlus}
        disabled={props.disablePlus}
        compact={true}
        autoRepressConfig={{
          dropoffMultiplier: 0.7,
          startingThreshold: 300,
          startingRepressTime: 150,
          minTimeMs: 50,
        }}
        mode="contained"
        size={20}
        ButtonComponent={IconButton}
        icon="plus"
      ></Button>
    ),
    [props.onPressPlus, props.disablePlus],
  );
  return useMemo(
    () => (
      <View style={styles.row}>
        {minusButton}
        {plusButton}
      </View>
    ),
    [minusButton, plusButton],
  );
}

const styles = StyleSheet.create({
  plusMinusAllocatorButton: {
    height: 32,
    width: 32,
    // borderRadius: 16,
    marginHorizontal: 4,
    marginVertical: 0,
    padding: 0,
  },
  plusMinusAllocatorButtonInner: {
    paddingVertical: 0,
    paddingHorizontal: 0,
    marginVertical: 0,
    marginHorizontal: 0,
    height: 32,
    width: 32,
  },
  plusMinusAllocatorIcon: {
    height: 20,
    width: 20,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
});
