import React, { useMemo } from "react";
import {
  I18nManager,
  Text as NativeText,
  StyleProp,
  StyleSheet,
  TextStyle,
} from "react-native";
import { useTheme } from "react-native-paper";
import { TextRef } from "react-native-paper/lib/typescript/src/components/Typography/Text";
import { VariantProp } from "react-native-paper/lib/typescript/src/components/Typography/types";
import { ThemeProp } from "react-native-paper/lib/typescript/src/types";

export type Props = React.ComponentProps<typeof NativeText> & {
  /**
   * @supported Available in v5.x with theme version 3
   *
   * Variant defines appropriate text styles for type role and its size.
   * Available variants:
   *
   *  Display: `displayLarge`, `displayMedium`, `displaySmall`
   *
   *  Headline: `headlineLarge`, `headlineMedium`, `headlineSmall`
   *
   *  Title: `titleLarge`, `titleMedium`, `titleSmall`
   *
   *  Label:  `labelLarge`, `labelMedium`, `labelSmall`
   *
   *  Body: `bodyLarge`, `bodyMedium`, `bodySmall`
   */
  variant?: VariantProp<string>;
  children: React.ReactNode;
  theme?: ThemeProp;
  style?: StyleProp<TextStyle>;
  h1?: boolean;
  h2?: boolean;
  h3?: boolean;
  h4?: boolean;
  doNotScaleFonts?: boolean;
};

const PaperText = (
  { style, variant, h1, h2, h3, h4, doNotScaleFonts, ...rest }: Props,
  ref: TextRef,
) => {
  const theme = useTheme();
  const writingDirection = I18nManager.getConstants().isRTL ? "rtl" : "ltr";

  const ariaLevel = h1 ? 1 : h2 ? 2 : h3 ? 3 : h4 ? 4 : undefined;
  const computedStyle = useMemo(
    () => [
      h1 && styles.h1,
      h2 && styles.h2,
      h3 && styles.h3,
      h4 && styles.h4,
      style,
    ],
    [h1, h2, h3, h4, style],
  );

  const fullStyle = useMemo(() => {
    if (variant) {
      return [
        theme.fonts[variant],
        styles.text,
        { writingDirection, color: theme.colors.onSurface },
        computedStyle,
      ];
    } else {
      const font = theme.fonts.default;
      const textStyle = {
        ...font,
        color: theme.colors?.onSurface,
      };
      return [styles.text, textStyle, { writingDirection }, computedStyle];
    }
  }, [theme, writingDirection, computedStyle]);

  if (variant) {
    return (
      <NativeText
        // @ts-ignore Not sure why this is giving error now
        style={fullStyle}
        maxFontSizeMultiplier={doNotScaleFonts === true ? 1.0 : 0}
        accessibilityRole={h1 || h2 || h3 || h4 ? "header" : undefined}
        selectable={false}
        // @ts-ignore We want to send this prop anyway
        ariaLevel={ariaLevel}
        accessibilityLevel={ariaLevel}
        aria-level={ariaLevel}
        {...rest}
      />
    );
  } else {
    return (
      <NativeText
        accessibilityRole={h1 || h2 || h3 || h4 ? "header" : undefined}
        selectable={false}
        // @ts-ignore We want to send this prop anyway
        ariaLevel={ariaLevel}
        accessibilityLevel={ariaLevel}
        aria-level={ariaLevel}
        maxFontSizeMultiplier={doNotScaleFonts === true ? 1.0 : 0}
        {...rest}
        // @ts-ignore Not sure why this is giving error now
        style={fullStyle}
      />
    );
  }
};

const Text = React.memo<Props>(PaperText);

export { Text };

