const styles = StyleSheet.create({
  h1: {
    fontSize: 20,
  },
  h2: {
    fontSize: 18,
  },
  h3: {
    fontSize: 16,
  },
  h4: {
    fontSize: 14,
  },
  text: {
    textAlign: "left",
  },
});
