import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { efficientExploration } from "./EfficientExploration";

class Compass extends Enhancement {
  getId(): string {
    return "compass";
  }
  getBaseName(): string {
    return t("Compass");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_EXPLORATION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 4500,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 5,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [efficientExploration];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "A compass while exploring means you will find your way much quicker and get lost much less.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("-30% Exploration delay");
  }
}

export const compass = new Compass();

registerTransformation(
  [[TransformationTags.ExplorationDelay]],
  compass.getId(),
  compass.getBaseName(),
  TransformationType.Multiplier,
  (state) => (compass.isPurchased(state) ? 0.7 : 1.0),
);
