import React, { useContext, useEffect, useMemo } from "react";
import { StyleSheet, View } from "react-native";
import {
  getCurrentEnemyAction,
  getEnemyActionProgress,
} from "../../backend/exploration/Exploration";
import { AccessibilityContext } from "../utility/AccessibilityContext";
import { ProgressBar } from "../utility/ProgressBar";
import { Text } from "../utility/Text";
import { UIContext } from "../utility/UIContext";
import { VisibilityContext } from "../utility/VisibilityContext";
import { GameIcon } from "../utility/icons/GameIcon";
import { useGameState } from "../utility/useGameState";

export function EnemyActionDisplay() {
  const state = useGameState();
  const uiContext = useContext(UIContext);
  const currentEventOccurrenceId = uiContext.getUIState(
    "current_event_occurrence_id",
  );
  const currentEnemyAction = getCurrentEnemyAction(state);
  const progress = getEnemyActionProgress(state);
  const accessibilityContext = useContext(AccessibilityContext);
  const visible = useContext(VisibilityContext);
  useEffect(() => {
    if (
      currentEnemyAction?.name != null &&
      currentEventOccurrenceId == null &&
      visible
    ) {
      setTimeout(
        () =>
          accessibilityContext.announceForAccessibility(
            currentEnemyAction?.name,
          ),
        0,
      );
    }
  }, [
    currentEnemyAction,
    currentEventOccurrenceId,
    accessibilityContext,
    visible,
  ]);
  return useMemo(() => {
    if (!currentEnemyAction) {
      return (
        <View style={styles.row}>
          <GameIcon icon="attack" style={styles.icon} />
          <View style={styles.progressBarContainer}>
            <Text style={styles.idleTextContainer}>-</Text>
            <ProgressBar progress={0} style={{ width: 120 }} />
          </View>
        </View>
      );
    }
    return (
      <View style={styles.row}>
        <GameIcon icon="attack" style={styles.icon} />
        <View style={styles.progressBarContainer}>
          <Text numberOfLines={1} style={styles.markdownContainer}>
            {currentEnemyAction.name}
          </Text>
          <View style={{ width: 120 }}>
            <ProgressBar progress={progress} style={{ width: 120 }} />
          </View>
        </View>
      </View>
    );
  }, [currentEnemyAction?.name, progress]);
}

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginRight: 4,
    height: 24,
    width: 24,
  },
  progressBarContainer: {
    flex: 1,
    marginTop: -5,
  },
  idleTextContainer: {
    height: 21,
  },
  markdownContainer: {
    height: 21,
  },
});
