import { memo } from "react";
import { View } from "react-native";
import { SYNCHRO_GRID_AMOUNT } from "../../backend/synchro/Synchro";
import { t } from "../../utils/I18n";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { SynchroGridSelector } from "./SynchroGridSelector";

export const SynchroGridSelectors = memo((props: {}) => {
  const selectors = [];
  for (let i = 0; i < SYNCHRO_GRID_AMOUNT; i++) {
    selectors.push(<SynchroGridSelector key={i} gridIndex={i} />);
  }

  return (
    <View style={Styles.flexRowAlignCenter}>
      <View>
        <Text h4>{t("Grids:")}</Text>
      </View>
      <View
        style={[
          Styles.ml16,
          Styles.flexRowAlignCenter,
          { flex: 1, gap: 4, flexWrap: "wrap" },
        ]}
      >
        {selectors}
      </View>
    </View>
  );
});
