import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Enemy } from "../../enemies/Enemy";
import EtherealEel from "../../enemies/sewers/EtherealEel";
import GoblinDoctor from "../../enemies/sewers/GoblinDoctor";
import GoblinSharpshooter from "../../enemies/sewers/GoblinSharpshooter";
import { manEatingPlant } from "../../enemies/sewers/ManEatingPlant";
import Sicklefish from "../../enemies/sewers/Sicklefish";
import VenomousSpider from "../../enemies/sewers/VenomousSpider";
import {
  DungeonFloor,
  ExplorationPossibility,
  encounterEnemy,
} from "../DungeonFloor";
import { DungeonFloors, isDungeonFloorCompletedThisRun } from "../Dungeons";

const EXPLORATION_POSSIBILITIES = [
  {
    weight: 30,
    transforms: encounterEnemy(GoblinSharpshooter),
  },
  {
    weight: 100,
    transforms: encounterEnemy(GoblinDoctor),
  },
  {
    weight: 20,
    transforms: encounterEnemy(Sicklefish),
  },
  {
    weight: 100,
    transforms: encounterEnemy(VenomousSpider),
  },
  {
    weight: 1,
    transforms: encounterEnemy(EtherealEel),
  },
];
class SewerHideout extends DungeonFloor {
  getId(): string {
    return "sewerHideout";
  }
  getFloorName(): string {
    return t("Sewer Hideout");
  }
  getBoss(state: GameState): Enemy {
    return manEatingPlant;
  }
  getBreedingLevel(): number {
    return 16;
  }
  isMainStoryDungeonFloor(): boolean {
    return true;
  }
  getExplorationRequiredTimeSecBase(): number {
    return 5.5;
  }
  _getExplorationSuccessesForBossBase(): number {
    return 30;
  }
  getBaseExplorationPossibilities(state: GameState): ExplorationPossibility[] {
    return EXPLORATION_POSSIBILITIES;
  }
  isUnlocked(state: GameState): boolean {
    return isDungeonFloorCompletedThisRun(state, "sewerHazardousSector");
  }
  getNextFloor(state: GameState): DungeonFloor {
    // Indirect reference due to demo
    return DungeonFloors.getByIdNullable("vastDesertFloor");
  }
}

export default new SewerHideout();
