import { valuesOf } from "../../utils/CoreUtils";
import { isIdentifiable } from "./Identifiable";

export class DataStore<T> {
  store: Record<string, T> = {};
  numericIds: Record<string, number> = {};
  maxId: number = 1;

  register(value: T, overrideId?: string) {
    const numericId = this.maxId++;
    let id = numericId.toString();
    if (isIdentifiable(value)) {
      id = value.getId();
    }
    if (overrideId != null) {
      id = overrideId;
    }
    if (this.store[id] == null) {
      this.store[id] = value;
    }
    if (this.numericIds[id] == null) {
      this.numericIds[id] = numericId;
    }
  }

  getById(id: string): T {
    return this.store[id];
  }

  getByIdNullable(id: string): T | undefined {
    return this.store[id];
  }

  getNumericId(id: string): number | undefined {
    return this.numericIds[id];
  }

  getAll(): T[] {
    return valuesOf(this.store);
  }

  reset(): void {
    this.maxId = 1;
    this.store = {};
  }
}
