import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { Spell } from "../../spells/Spell";
import {
  AutocastCondition,
  AutocastConditionParameter,
} from "../AutocastCondition";

class SmartlyAutocastCondition extends AutocastCondition {
  getId(): string {
    return "Smartly";
  }
  getDisplayName(): string {
    return t("Smartly");
  }
  getParameters(): AutocastConditionParameter[] {
    return [];
  }
  getDisplayExplanation(state: GameState, values: string[]): string {
    return t("Smartly");
  }
  isConditionMet(
    state: GameState,
    spell: Spell | undefined,
    values: string[],
  ): boolean {
    if (spell == null) {
      return true;
    }
    return spell.isCastingRecommended(state);
  }
  validate(state: GameState, values: string[]): Record<number, string> {
    return {};
  }
}

export default new SmartlyAutocastCondition();
