import { loadRandomEventTriggers } from "./autorandom/RandomEventTrigger";
import { loadRandomRepeatableEvents } from "./autorandom/RandomRepeatableEvents";
import { loadDemoEndEvent } from "./DemoEndEvent";
import { loadDungeonEventGroups } from "./DungeonEventGroup";
import { loadProgressionEventsDemo } from "./progression/DemoProgressionEvents";
import { loadLevelUpEvents } from "./progression/LevelUp";
import { loadStorylineEventsDemo } from "./storyline/DemoStorylinesLoader";

export function loadGameEventsDemo() {
  loadDungeonEventGroups();
  loadRandomEventTriggers();

  // Events below
  loadProgressionEventsDemo();
  loadLevelUpEvents();
  loadStorylineEventsDemo();
  loadRandomRepeatableEvents();

  loadDemoEndEvent();
}
