import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import {
  getEnemyTurnCounter,
  resetEnemyNextAction,
  resetFamiliarNextAction,
} from "../exploration/Exploration";
import { ExplorationStatus } from "../exploration/ExplorationStatus";
import { getExplorationStatus } from "../exploration/ExplorationUtils";
import { setCombatVariable } from "../Flags";
import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectEndTime,
  grantTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Stunned extends TemporaryEffect {
  getId(): string {
    return "stun";
  }
  getDisplayName(): string {
    return t("Stunned!");
  }
  isBeneficial(): boolean {
    return false;
  }
  getIcon() {
    return require("./stunned.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Cannot use spells or attack; Current attack is canceled");
  }
}

export const stunned = new Stunned();

class StunProtection extends TemporaryEffect {
  getId(): string {
    return "stunProtection";
  }
  getDisplayName(): string {
    return t("Stun Protection");
  }
  isBeneficial(): boolean {
    return true;
  }
  getIcon() {
    return require("./stunProtection.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t('Immune to "Stunned!"');
  }
}

export const stunProtection = new StunProtection();

export function loadStunning() {
  TemporaryEffects.register(stunned);
  TemporaryEffects.register(stunProtection);

  // While stunned, enemies and players both have a giant attack delay
  registerTransformation(
    [[AttackTarget.Player, CombatStat.AttackDelay]],
    "PlayerStunAttackDelay",
    t("Stunned!"),
    TransformationType.Override,
    (state) =>
      hasTemporaryEffect(state, stunned.getId(), AttackTarget.Player)
        ? +Infinity
        : null,
  );
  registerTransformation(
    [[AttackTarget.Familiar, CombatStat.AttackDelay]],
    "FamiliarStunAttackDelay",
    t("Stunned!"),
    TransformationType.Override,
    (state) =>
      hasTemporaryEffect(state, stunned.getId(), AttackTarget.Familiar)
        ? +Infinity
        : null,
  );
  registerTransformation(
    [[AttackTarget.Enemy, CombatStat.AttackDelay]],
    "EnemyStunAttackDelay",
    t("Stunned!"),
    TransformationType.Override,
    (state) =>
      hasTemporaryEffect(state, stunned.getId(), AttackTarget.Enemy)
        ? +Infinity
        : null,
  );

  registerTemporaryEffectCombatStatCacheClearer(
    stunned.getId(),
    CombatStat.AttackDelay,
  );

  registerTemporaryEffectListener("stunning", (newState, temporaryEffect) => {
    if (
      temporaryEffect.id === stunned.getId() &&
      temporaryEffect.endTime > temporaryEffect.setTime
    ) {
      // Someone's been stunned, but does it have temporary immunity?
      if (
        hasTemporaryEffect(
          newState,
          stunProtection.getId(),
          temporaryEffect.target,
        )
      ) {
        temporaryEffect.endTime = getTemporaryEffectEndTime(
          newState,
          stunned.getId(),
          temporaryEffect.target,
        );
        return temporaryEffect;
      }
    }
    return temporaryEffect;
  });

  addGameStateListener(
    "PlayerStunning",
    (newState) => {
      if (hasTemporaryEffect(newState, stunned.getId(), AttackTarget.Player)) {
        const stunDuration =
          getTemporaryEffectEndTime(
            newState,
            stunned.getId(),
            AttackTarget.Player,
          ) - getSecondsPlayed(newState);

        // Player's stun has been set. Set attack timer start time to estimated stun end
        // so that on heal we don't immediately attack. Also give stun protection.
        return (state) => {
          state.run.exploration.playerActionProgress = 0.0;
          state = grantTemporaryEffect(
            stunProtection.getId(),
            applyTransformations(
              [
                AttackTarget.Player,
                TransformationTags.PostStunProtectionDuration,
              ],
              newState,
              6.0,
            ) + stunDuration,
            { mode: "add", target: AttackTarget.Player },
          )(state);
          return state;
        };
      }
    },
    (state) =>
      getTemporaryEffectEndTime(state, stunned.getId(), AttackTarget.Player),
  );

  addGameStateListener(
    "PlayerStunningRecovery",
    (newState) => {
      if (!hasTemporaryEffect(newState, stunned.getId(), AttackTarget.Player)) {
        // Player's stun has run out, so restart the attack timer
        return (state) => {
          state.run.exploration.playerActionProgress = 0.0;
          return state;
        };
      }
    },
    (state) => hasTemporaryEffect(state, stunned.getId(), AttackTarget.Player),
  );

  addGameStateListener(
    "FamiliarStunning",
    (newState) => {
      if (
        hasTemporaryEffect(newState, stunned.getId(), AttackTarget.Familiar)
      ) {
        const stunDuration =
          getTemporaryEffectEndTime(
            newState,
            stunned.getId(),
            AttackTarget.Familiar,
          ) - getSecondsPlayed(newState);

        // Player's stun has been set. Set attack timer start time to estimated stun end
        // so that on heal we don't immediately attack. Also give stun protection.
        return (state) => {
          state.run.exploration.familiarActionProgress = 0.0;
          state = grantTemporaryEffect(
            stunProtection.getId(),
            applyTransformations(
              [
                AttackTarget.Familiar,
                TransformationTags.PostStunProtectionDuration,
              ],
              newState,
              6.0,
            ) + stunDuration,
            { mode: "add", target: AttackTarget.Familiar },
          )(state);
          return state;
        };
      }
    },
    (state) =>
      getTemporaryEffectEndTime(state, stunned.getId(), AttackTarget.Familiar),
  );

  addGameStateListener(
    "FamiliarStunningRecovery",
    (newState) => {
      if (
        !hasTemporaryEffect(newState, stunned.getId(), AttackTarget.Familiar)
      ) {
        // Player's stun has run out, so restart the attack timer
        return (state) => {
          resetFamiliarNextAction();
          state.run.exploration.familiarActionProgress = 0.0;
          return state;
        };
      }
    },
    (state) =>
      hasTemporaryEffect(state, stunned.getId(), AttackTarget.Familiar),
  );

  addGameStateListener(
    "EnemyStunning",
    (newState) => {
      if (hasTemporaryEffect(newState, stunned.getId(), AttackTarget.Enemy)) {
        const stunDuration =
          getTemporaryEffectEndTime(
            newState,
            stunned.getId(),
            AttackTarget.Enemy,
          ) - getSecondsPlayed(newState);

        // Enemy's stun has been set. Set attack timer start time to estimated stun end
        // so that on heal we don't immediately attack. Also give stun protection.
        // Also advance turn counter by 1.
        return (state) => {
          state.run.exploration.enemyActionProgress = 0.0;
          state = setCombatVariable(
            "enemyTurnCounter",
            getEnemyTurnCounter(state) + 1,
          )(state);
          if (getExplorationStatus(state) === ExplorationStatus.Combat) {
            state = grantTemporaryEffect(
              stunProtection.getId(),
              applyTransformations(
                [
                  AttackTarget.Enemy,
                  TransformationTags.PostStunProtectionDuration,
                ],
                newState,
                6.0,
              ) + stunDuration,
              { mode: "add", target: AttackTarget.Enemy },
            )(state);
          }
          return state;
        };
      }
    },
    (state) =>
      getTemporaryEffectEndTime(state, stunned.getId(), AttackTarget.Enemy),
  );

  addGameStateListener(
    "EnemyStunningRecovery",
    (newState) => {
      if (!hasTemporaryEffect(newState, stunned.getId(), AttackTarget.Enemy)) {
        // Enemy's stun has run out, so restart the attack timer
        return (state) => {
          resetEnemyNextAction();
          state.run.exploration.enemyActionProgress = 0.0;
          return state;
        };
      }
    },
    (state) => hasTemporaryEffect(state, stunned.getId(), AttackTarget.Enemy),
  );
}
