import { t } from "../../../utils/I18n";
import { CombatClassesStoryline } from "../../classes/CombatClassesStoryline";
import { isDungeonFloorCompletedThisRun } from "../../exploration/dungeons/Dungeons";
import SewerEntrance from "../../exploration/dungeons/sewers/SewerEntrance";
import SewerHazardousSector from "../../exploration/dungeons/sewers/SewerHazardousSector";
import SewerHideout from "../../exploration/dungeons/sewers/SewerHideout";
import { addEnemyDeathListener } from "../../exploration/Exploration";
import { Flag } from "../../Flag";
import { hasFlag, setFlag, SetFlagListeners } from "../../Flags";
import {
  getUnlockedElements,
  partiallyUnlockElement,
  SpellElement,
} from "../../spells/Elements";
import { buildEvent } from "../EventMessageBuilder";
import { triggerEvent } from "../Events";
import { EventTag } from "../GameEvent";

export function loadSewersProgression() {
  const sewersUnlockEventBuilder = buildEvent(
    "sewersUnlock",
    t("A Stinky Adventure"),
    [EventTag.MAIN_QUEST],
  );

  sewersUnlockEventBuilder.message(
    t(`With your combat abilities, you now feel ready to begin your actual search for the Philosopher's Stone. You start thinking about what should be the next area you should be heading to, when you hear a knock on your door.
  
When you check, there is no one... Oddly, you open the door, and you notice a pamphlet on the ground. "*Wanted: Mad Alchemist*", it says, with a crude drawing of a deformed person. You take it, and show it to your hired wizard.`),
  );
  sewersUnlockEventBuilder
    .message(
      t(`"A wanted poster? We get several of those, fairly often, at the guild", she mentions. "It looks like this one references a nefarious alchemist that seems to be running experiments somewhere in the town sewers. It appears the wells in some places in town have become poisoned, and the town suspects this alchemist."
  
Then something she says catches your attention: "It says here that the experiments have something to do with creating a Philosopher's Stone..." *Your blood freezes.* You ask her to repeat and confirm the fact. She is not lying to you: that is what it says on the paper.`),
    )
    .option(t("Continue"), { transform: setFlag(Flag.SewersUnlocked) }, "end");
  sewersUnlockEventBuilder
    .message(
      t(`It surely can't be... out of all the places in the world where you could be starting your search, the *sewers* are your first lead?
  
Your hired wizard seems to have noticed your reaction, and makes a face of disgust. "You can't possibly be thinking of *going in there*, can you?"

But you are... you certainly are. Or so you tell yourself, conflicted, as you try to convince yourself that your loved one's illness was much worse than *just a stench*...

**You can now explore the Sewers!**`),
    )
    .tag("end");

  const sewersUnlockEvent = sewersUnlockEventBuilder.build();

  SetFlagListeners.register((state, flag) => {
    if (
      ((flag == Flag.EnhancementsUnlocked &&
        CombatClassesStoryline.isCompleted(state)) ||
        flag == CombatClassesStoryline.getCompletedFlag()) &&
      !hasFlag(state, Flag.SewersUnlocked) &&
      !sewersUnlockEvent.hasTriggered(state)
    ) {
      return triggerEvent(sewersUnlockEvent)(state);
    }
    return state;
  }, "sewersUnlockEventTriggerer");

  const sewersEntranceBossUnlockEventBuilder = buildEvent(
    "sewersEntranceBossUnlock",
    t("A Challenger from the Deep?"),
    [EventTag.MAIN_QUEST],
  );

  sewersEntranceBossUnlockEventBuilder.message(
    t(`While exploring, you find a large circular room filled with shallow water and a tunnel entrance on the other side. Your hired wizard comments: "I think all paths end up coming here".
  
As you begin crossing, you are stopped on your tracks. There is something in the water. It looks a little like a Sewer Fish, but it is green and deformed - and very aggressive, moving faster than what you are used to. You ready for combat.

**You have unlocked the boss of Sewer Entrance!**`),
  );

  const sewersEntranceBossUnlockEvent =
    sewersEntranceBossUnlockEventBuilder.build();

  addEnemyDeathListener(
    sewersEntranceBossUnlockEvent.getId() + "Triggerer",
    (state) => {
      if (
        !sewersEntranceBossUnlockEvent.hasTriggered(state) &&
        SewerEntrance.canFightBoss(state)
      ) {
        return triggerEvent(sewersEntranceBossUnlockEvent)(state);
      }
      return state;
    },
  );

  const sewersEntranceBossDefeatedMainEventBuilder = buildEvent(
    "sewersEntranceBossDefeatedMain",
    t("Mimicking the Poison"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger((state) => {
    state = partiallyUnlockElement(SpellElement.Poison)(state);
    return state;
  });

  sewersEntranceBossDefeatedMainEventBuilder.message(
    t(`You defeat the Mutated Fish! Your hired wizard yells from the thrill. You check the floating corpse one last time to make sure it is truly dead. Although you can move on, you are noticing that the sewer enemies seem to be able to use a different type of magic entirely. A different *element*, perhaps? You are not sure about it, but you want to try and mimic it.

**You have discovered the "Poison" element! It is only *partially unlocked*, so it will not persist through retirement, and you will have a very hard time using it.**

**You can now also access a new Exploration area: "Hazardous Sector"!**`),
  );

  const sewersEntranceBossDefeatedMainEvent =
    sewersEntranceBossDefeatedMainEventBuilder.build();

  const sewersEntranceBossDefeatedAltEventBuilder = buildEvent(
    "sewersEntranceBossDefeatedAlt",
    t("Hazardous Sector"),
    [EventTag.MAIN_QUEST],
  );

  sewersEntranceBossDefeatedAltEventBuilder.message(
    t(`You defeat the Mutated Fish! Your hired wizard yells from the thrill. You check the floating corpse one last time to make sure it is truly dead, and after doing so, move on past it to the next area of the Sewers.

**You can now also access a new Exploration area: "Hazardous Sector"!**`),
  );

  const sewersEntranceBossDefeatedAltEvent =
    sewersEntranceBossDefeatedAltEventBuilder.build();

  addEnemyDeathListener(
    sewersEntranceBossDefeatedMainEvent.getId() + "Triggerer",
    (state) => {
      if (
        !sewersEntranceBossDefeatedAltEvent.hasTriggered(state) &&
        !sewersEntranceBossDefeatedMainEvent.hasTriggered(state) &&
        isDungeonFloorCompletedThisRun(state, SewerEntrance)
      ) {
        if (getUnlockedElements(state).includes(SpellElement.Poison)) {
          return triggerEvent(sewersEntranceBossDefeatedAltEvent)(state);
        } else {
          return triggerEvent(sewersEntranceBossDefeatedMainEvent)(state);
        }
      }
      return state;
    },
  );

  const hazardousSectorBossDefeatedMainEventBuilder = buildEvent(
    "hazardousSectorBossDefeatedMain",
    t("Hoarding Monstium"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.MonstruariumsUnlocked));

  hazardousSectorBossDefeatedMainEventBuilder.message(
    t(`The Sludge Mass is finally no more! You still feel somewhat sick; you try and catch your breath in the corner of the room, waiting until it gets better. You notice it left behind a big pile of :monstium:, and wonder - it would be great if you could store more of this resource. You then realize that perhaps there could be a way to have a storage, similarly to how this enemy did it - leveraging Poison (or :poisonessence:) to hold it together...

**You can now build Monstruariums, a new furniture that allows you to store more :monstium:!**

**You can now also access a new Exploration area: "The Hideout"!**`),
  );

  const hazardousSectorBossDefeatedMainEvent =
    hazardousSectorBossDefeatedMainEventBuilder.build();

  addEnemyDeathListener(
    hazardousSectorBossDefeatedMainEvent.getId() + "Triggerer",
    (state) => {
      if (
        !hasFlag(state, Flag.MonstruariumsUnlocked) &&
        !hazardousSectorBossDefeatedMainEvent.hasTriggered(state) &&
        isDungeonFloorCompletedThisRun(state, SewerHazardousSector)
      ) {
        return triggerEvent(hazardousSectorBossDefeatedMainEvent)(state);
      }
      return state;
    },
  );

  const sewersHideoutBossUnlockEventBuilder = buildEvent(
    "sewersHideoutBossUnlock",
    t("Carnivorous Guardian"),
    [EventTag.MAIN_QUEST],
  );

  sewersHideoutBossUnlockEventBuilder.message(
    t(`The Sewers have gotten deeper and deeper, and more dangerous. You are certain that by now, you must have explored almost every corner, but you haven't found the wanted alchemist just yet.
    
In what you feel is the last tunnel, you see a door. It appears to have been used recently, and you spot a light from the other side. But something is right in front of the door. There is a pile of... waste, and a giant Man-Eating Plant growing right on top of it. It takes notice of you and makes an awful sound - and it looks extremely dangerous. You are afraid, but you know you're going to have to get past it to get to that door. You get ready for a very tough battle...

**You have unlocked the boss of The Hideout!**`),
  );

  const sewersHideoutBossUnlockEvent =
    sewersHideoutBossUnlockEventBuilder.build();

  addEnemyDeathListener(
    sewersHideoutBossUnlockEvent.getId() + "Triggerer",
    (state) => {
      if (
        !sewersHideoutBossUnlockEvent.hasTriggered(state) &&
        SewerHideout.canFightBoss(state)
      ) {
        return triggerEvent(sewersHideoutBossUnlockEvent)(state);
      }
      return state;
    },
  );

  const sewersCompletedEventBuilder = buildEvent(
    "sewersCompleted",
    t("The Mad Alchemist"),
    [EventTag.MAIN_QUEST],
  );

  sewersCompletedEventBuilder.message(
    t(`Frantically moving left and right to try and avoid the multiple incoming attacks from each of the three heads, you deal one more blow in the tough fight. The plant gets pushed back and stops moving. Tricked twice already, you brace yourself, low in supplies, for a fourth head to pop up - but this time, it doesn't appear. You must have succeeded!
    
Your hired wizard jumps cheerfully and high-fives you. You are glad the fight is over - it was very difficult!

With the Man-Eating Plant out of the way, the door is now reachable. Inside must be the person you have been looking for all this time: the wanted "Mad Alchemist".`),
  );

  sewersCompletedEventBuilder.message(
    t(`"Is someone there?" The coarse voice comes from inside. "Come in."
    
Your hired wizard and you hesitantly enter. On the other side of the door is a room that is fancied like a study, or a lab perhaps - shelves full of potions and substances of various colors, several books, and a large desk in the center with flasks and other glass containers. On the other side of the desk is the so-called "Mad Alchemist": a man in their later years, with grey curly hair, a furrowed brow, and a peculiar scent.`),
  );

  sewersCompletedEventBuilder.message(
    t(`Your hired wizard begins talking: "Are you the one who has been poisoning..." You interrupt her. The reason you are here is not to apprehend the wanted man, even though it is possible that he has been running potentially harmful experiments in these sewers. Instead, you ask him about what has been in your mind all this time: the *Philosopher's Stone*.
    
"The Philosopher's Stone..." He begins to talk, and closes his eyes. "Do you really think I would be living here, in seclusion, away from people, if I had such an object?"

"The answer is obvious, but... no, I don't have a Philosopher's Stone."`),
  );

  sewersCompletedEventBuilder.message(
    t(
      `He continues to talk. "The Philosopher's Stone is the ultimate aspiration of any alchemist. It is only spoken of in legends, and as far as I know, nobody has been able to create such an item. But, what they say in the city is true: I have been trying, for many years, to create one, and yet - have not had any success."
      
You ask him for more details. He readily gives out an answer, but it is not exactly what you are looking for: "The Ancient Legends speak of a Philosopher's Stone that was given to the leader of Zeth, an ancient kingdom that used to be somewhere in the Vast Desert. Shortly after, the kingdom fell to unknown ruin. Nobody really knows what happened."`,
    ),
  );

  sewersCompletedEventBuilder.message(
    t(`He continues: "I have also considered exploring the Vast Desert, but... I am old, as you see, and quite frail now... not ready for the physical toughness and combat prowess that are needed there." He lets out a small sigh.
    
You symphatize with him. You do not know his motivation for the Philosopher's Stone, but you can tell he does not have many more years left to live.`),
  );

  sewersCompletedEventBuilder
    .message(
      t(`You close your eyes for a while. The Vast Desert... The place is actually quite close. You've heard it is quite dangerous, and have always been discouraged to step in there. The desert extends as far out as the eye can see, and there is no clear path. It is easy to get lost, and the conditions can be fairly extreme to endure.
    
You feel a renewed urge to search it. If the legends are true, you are bound to find clues about the Philosopher's Stone there. Or maybe even find the object itself...?

You tell the Alchemist this much, and he offers you a map of the Desert. But with one condition: to not turn him in to the authorities for what he has done. Without thinking, you accept, getting a stern look from your hired wizard, and take the map.`),
    )
    .option(
      t("Continue"),
      { transform: setFlag(Flag.VastDesertUnlocked) },
      "end",
    )
    .end();

  sewersCompletedEventBuilder
    .message(
      t(`As you receive the map, you thank the old man - though, you do ask him to try to be careful with the city's water supply.
    
And finally, you turn back and head out. You are ready to trip through the Vast Desert. You ask the hired wizard to follow you, and she replies enthusiastically: "Of course!" But she quickly adds to it, wrinkling her nose: "But only after taking a bath..."

**You can now explore the Vast Desert!**`),
    )
    .tag("end");

  const sewersCompletedEvent = sewersCompletedEventBuilder.build();

  addEnemyDeathListener(sewersCompletedEvent.getId() + "Triggerer", (state) => {
    if (
      !sewersCompletedEvent.hasTriggered(state) &&
      isDungeonFloorCompletedThisRun(state, SewerHideout)
    ) {
      return triggerEvent(sewersCompletedEvent)(state);
    }
    return state;
  });
}
