import { Entypo } from "@expo/vector-icons";
import React, { useMemo } from "react";
import { StyleSheet, TouchableWithoutFeedback, View } from "react-native";
import { useTheme } from "react-native-paper";
import { getCumulativeLosses } from "../../backend/exploration/Exploration";
import { getUnlockedDungeons } from "../../backend/exploration/dungeons/Dungeons";
import {
  BestiaryState,
  Enemy,
  getCumulativeEnemyKills,
  getCumulativeEnemyKillsFromCreatures,
} from "../../backend/exploration/enemies/Enemy";
import { Items } from "../../backend/items/Items";
import { RoundingMethod, formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { EnemySpriteTile } from "../exploration/EnemySpriteTile";
import CategoryContainer from "./CategoryContainer";
import { EnemyCombatStatsViewer } from "./CombatStatsViewer";
import { Markdown } from "./Markdown";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

function BestiaryEnemyContents(props: { enemy: Enemy }) {
  const enemy = props.enemy;
  const id = enemy.getId();
  const name = enemy.getName();
  const state = useGameState();

  const unlockedFloors = getUnlockedDungeons(state)
    .map((dungeon) =>
      dungeon.getDungeonFloors().filter((floor) => floor.isUnlocked(state)),
    )
    .flat();

  const bestiaryState = enemy.getBestiaryState(state);

  const foundInDungeons = unlockedFloors
    .filter((floor) =>
      floor
        .getExplorationPossibilities(state, false)
        .find((possibility) => possibility.transforms?.enemy == enemy),
    )
    .map((floor) => {
      const possibilities = floor.getBaseExplorationPossibilities(state);
      const weight =
        possibilities.find(
          (possibility) => possibility.transforms?.enemy == enemy,
        )?.weight ?? 0;
      const totalWeight = possibilities.reduce(
        (prev, current) => prev + current.weight,
        0,
      );
      return `*${floor.getFloorName()}*: ${formatNumber(
        (weight * 100) / totalWeight,
        { showDecimals: true },
      )}%`;
    })
    .concat(
      unlockedFloors
        .filter((floor) => floor.getBoss(state) == enemy)
        .map((floor) =>
          t("*{{dungeonFloorName}}* (Boss)", {
            dungeonFloorName: floor.getFloorName(),
          }),
        ),
    )
    .join(t(", "));

  const showDrops = bestiaryState == BestiaryState.Loot;

  const dropsText = [
    enemy.getCoinsAwardedBase(state) > 0
      ? t("{{coinsAwarded}}:coins:", {
          coinsAwarded: formatNumber(enemy.getCoinsAwardedBase(state)),
        })
      : null,
    enemy.getMonstiumAwardedBase(state) > 0
      ? t("{{monstiumAwarded}}:monstium:", {
          monstiumAwarded: formatNumber(enemy.getMonstiumAwardedBase(state)),
        })
      : null,
  ]
    .concat(
      enemy.getItemsAwardedBase(state).map((loot) => {
        if (loot.chance == 0) {
          return null;
        }
        const item = Items.getById(loot.itemId);
        return t("{{amount}}x{{itemIcon}}*{{itemName}}* ({{lootChancePct}}%)", {
          amount: loot.amount,
          itemIcon: `:${loot.itemId}:`,
          itemName: item.getName(loot.params ?? item.getDefaultParams()),
          lootChancePct: formatNumber(loot.chance * 100, {
            showDecimals: true,
            roundingMethod: RoundingMethod.UP,
          }),
        });
      }),
    )
    .filter((v) => v != null)
    .join(t(", "));

  const cumulativeKills = getCumulativeEnemyKills(state, enemy.getId());
  const cumulativeLosses = getCumulativeLosses(state, enemy.getId());
  const cumulativeCreatureKills = getCumulativeEnemyKillsFromCreatures(
    state,
    enemy.getId(),
  );

  const generalText1 = t("Times defeated: {{cumulativeKills}}", {
    cumulativeKills: formatNumber(cumulativeKills),
  });
  const generalText2 = t("Times you ran away: {{cumulativeLosses}}", {
    cumulativeLosses: formatNumber(cumulativeLosses),
  });

  const showEnemyStats = bestiaryState != BestiaryState.SpriteOnly;
  const showTip =
    bestiaryState != BestiaryState.SpriteOnly && enemy.getTips() != null;
  const tips = enemy.getTips();

  const enemyContentContainer = useMemo(() => {
    return (
      <View style={styles.contentContainer}>
        <View style={styles.row}>
          <View style={styles.column}>
            <EnemySpriteTile sprite={enemy.getPicture(state)} />
          </View>
          <View style={styles.column}>
            <CategoryContainer title={t("Base stats")}>
              <View style={styles.categoryContents}>
                <EnemyCombatStatsViewer
                  enemy={enemy}
                  obfuscateValues={!showEnemyStats}
                />
              </View>
            </CategoryContainer>
            <CategoryContainer title={t("General")}>
              <View style={styles.categoryContents}>
                <Markdown>{generalText1}</Markdown>
                <Markdown>{generalText2}</Markdown>
              </View>
            </CategoryContainer>
          </View>
        </View>
        <View style={styles.row}>
          <View style={styles.column}>
            <CategoryContainer title={t("Found in")}>
              <View style={styles.categoryContents}>
                <Markdown>
                  {foundInDungeons != "" ? foundInDungeons : t("???")}
                </Markdown>
              </View>
            </CategoryContainer>
          </View>
          <View style={styles.column}>
            <CategoryContainer title={t("Base loot")}>
              <View style={styles.categoryContents}>
                <Markdown>
                  {!showDrops
                    ? t(
                        "You must defeat this enemy more times to see loot information!",
                      )
                    : dropsText != ""
                    ? dropsText
                    : t("No loot")}
                </Markdown>
              </View>
            </CategoryContainer>
          </View>
        </View>
        {showTip && (
          <View>
            <CategoryContainer title={t("Combat tips (from hired wizard)")}>
              <View style={styles.categoryContents}>
                <Markdown>{tips}</Markdown>
              </View>
            </CategoryContainer>
          </View>
        )}
      </View>
    );
  }, [
    enemy,
    foundInDungeons,
    dropsText,
    generalText1,
    generalText2,
    showTip,
    tips,
    showEnemyStats,
  ]);

  return enemyContentContainer;
}

export function BestiaryEnemyContainer(props: {
  enemy: Enemy;
  collapsed?: boolean;
  onPressExpand?: () => void;
}) {
  const enemy = props.enemy;
  const id = enemy.getId();
  const name = enemy.getName();

  const theme = useTheme();

  return useMemo(() => {
    return (
      <View style={styles.helpContainer} key={id}>
        <TouchableWithoutFeedback
          accessibilityRole="button"
          accessibilityHint="Expand"
          onPress={props.onPressExpand}
        >
          <View style={styles.topRowContainer}>
            <Text h3 key={id} style={[styles.topContainer]}>
              {t("{{enemyName}}{{bossText}}", {
                enemyName: name,
                bossText: enemy.isBoss() ? t(" (Boss)") : "",
              })}
            </Text>
            <View style={styles.expandContainer}>
              <Entypo
                name={props.collapsed ? "chevron-right" : "chevron-down"}
                size={24}
                color={theme.colors.onBackground}
              />
            </View>
          </View>
        </TouchableWithoutFeedback>
        {!props.collapsed && (
          <View>
            <BestiaryEnemyContents enemy={enemy} />
          </View>
        )}
      </View>
    );
  }, [theme, name, id, props.collapsed, props.onPressExpand]);
}

const styles = StyleSheet.create({
  helpContainer: { marginVertical: 8 },
  topRowContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    cursor: "pointer",
  },
  expandContainer: {
    width: 40,
  },
  descriptionTextContainer: {
    marginBottom: 8,
    marginTop: 8,
    marginHorizontal: 8,
    flex: 1,
    flexDirection: "row",
    flexWrap: "wrap",
  },
  descriptionViewer: { flex: 1, justifyContent: "center" },
  topContainer: {
    flex: 1,
    marginLeft: 8,
  },
  disabledStorylineText: {
    color: "#aaa",
  },
  contentContainer: {
    paddingVertical: 8,
  },
  row: {
    flexDirection: "row",
  },
  categoryContents: {
    marginTop: 8,
  },
  column: {
    flex: 1,
    marginHorizontal: 4,
  },
});
