import color from "color";
import * as Clipboard from "expo-clipboard";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { Card } from "react-native-paper";
import { formatMultiplier, formatNumber } from "../utils/FormattingUtils";
import { Card as UtilityCard } from "./utility/Card";
import { Markdown } from "./utility/Markdown";
import { Text } from "./utility/Text";

import { useTheme } from "react-native-paper";
import {
  BASE_BOON_POINTS_PER_WORLD,
  MAX_BOON_LEVEL,
  getBlazingSpeedLevel,
  getIncrementalPointsRequiredForLevel,
  getMaxPossibleDifficultyLevel,
  getNewBoonPointsAvailable,
  getNewGamePlusAccuracyAndDodgeMultiplier,
  getNewGamePlusAttackDelayMultiplier,
  getNewGamePlusBoonPointsEarnedMultiplier,
  getNewGamePlusChannelingAndSalePriceMultiplier,
  getNewGamePlusElementMaxLevelBonus,
  getNewGamePlusExpGainMultiplier,
  getNewGamePlusFamiliarMaxLevelBonus,
  getNewGamePlusHpMultiplier,
  getNewGamePlusItemQualityMaxBonus,
  getNewGamePlusLootMultiplier,
  getNewGamePlusOtherStatMultiplier,
  getNewGamePlusProductionMultiplier,
  getNewGamePlusStorageMultiplier,
  getPointsRequiredForConfiguration,
  getSheerPowerLevel,
  getStorylineOracleLevel,
  getStorylinesToUnlockForSpecificOracleLevel,
  getTotalAvailableBoonPoints,
  getTotalPointsRequiredForLevel,
  getWorldDifficultyLevel,
  triggerNewGamePlus,
} from "../backend/NewGamePlus";
import { serializeSaveData } from "../backend/SaveData";
import { FAMILIAR_LEVEL_CAP_BASE } from "../backend/familiars/FamiliarClass";
import { INITIAL_MAX_ITEM_QUALITY } from "../backend/items/Item";
import { ELEMENT_LEVEL_CAP_BASE } from "../backend/spells/Elements";
import { SAVE_TO_FILE_SUPPORTED, saveToFile } from "../utils/FilesystemSave";
import { t } from "../utils/I18n";
import { Button } from "./utility/Button";
import { PlusMinusAllocator } from "./utility/PlusMinusAllocator";
import Styles from "./utility/Styles";
import { VisibilityContext } from "./utility/VisibilityContext";
import { useGameState } from "./utility/useGameState";

const SheerPowerDescription = React.memo((props: { level: number }) => {
  const level = props.level;

  const explanationBase = (
    <Markdown>
      {t(
        `The Sheer Power Boon increases the limit of how strong you can be. It increases level and item quality caps and maximum Storage, as well as boosting production slightly across the board. At some point, if you hope to defeat ever more difficult foes, this Boon will be necessary.`,
      )}
    </Markdown>
  );

  if (level == 0) {
    return (
      <View>
        <View>{explanationBase}</View>
        <View>
          <Markdown>
            {t(`Your Sheer Power level will be 0. You will get no benefits.`)}
          </Markdown>
        </View>
      </View>
    );
  }

  const elementMaxLevelBonus = getNewGamePlusElementMaxLevelBonus(level);
  const familiarMaxLevelBonus = getNewGamePlusFamiliarMaxLevelBonus(level);
  const itemQualityBonus = getNewGamePlusItemQualityMaxBonus(level);

  return (
    <View>
      <View>{explanationBase}</View>
      <View>
        <Markdown>
          {t(
            `Your Sheer Power level will be {{level}}. You will get the following benefits:
    
* Your maximum Element level will become **{{maxElementLevel}}**;
* Your maximum Familiar level will become **{{maxFamiliarLevel}}**;
* The maximum item quality will become **+{{maxItemQuality}}**;
* **{{storageMultiplier}}** bonus to all Storage maximums (except :mana: and :timepiece:);
* **{{productionMultiplier}}** bonus to all Production`,
            {
              level: formatNumber(level),
              maxElementLevel: formatNumber(
                ELEMENT_LEVEL_CAP_BASE + elementMaxLevelBonus,
              ),
              maxFamiliarLevel: formatNumber(
                FAMILIAR_LEVEL_CAP_BASE + familiarMaxLevelBonus,
              ),
              maxItemQuality: formatNumber(
                INITIAL_MAX_ITEM_QUALITY + itemQualityBonus,
              ),
              storageMultiplier: formatMultiplier(
                getNewGamePlusStorageMultiplier(level),
              ),
              productionMultiplier: formatMultiplier(
                getNewGamePlusProductionMultiplier(level),
              ),
            },
          )}
        </Markdown>
      </View>
    </View>
  );
});

const StorylineOracleDescription = React.memo((props: { level: number }) => {
  const level = props.level;

  const explanationBase = (
    <Markdown>
      {t(
        `The Storyline Oracle Boon lets you manifest bonuses from Storylines that might originally have happened in the future, immediately at the beginning - effectively having a large impact in the early game. Obtain powerful bonuses without the need to progress throughout most of the game to unlock them!`,
      )}
    </Markdown>
  );

  if (level == 0) {
    return (
      <View>
        <View>{explanationBase}</View>
        <View>
          <Markdown>
            {t(
              `Your Storyline Oracle level will be 0. You will get no benefits.`,
            )}
          </Markdown>
        </View>
      </View>
    );
  }

  if (level == 10) {
    return (
      <View>
        <View>{explanationBase}</View>
        <View>
          <Markdown>
            {t(
              `Your Storyline Oracle level will be 10. This means **you will start with all Storylines, except endings and Challenges, already completed!**`,
            )}
          </Markdown>
        </View>
      </View>
    );
  }

  const storylineBonusesLines = [];
  for (let i = 1; i <= level; i++) {
    const storylines = getStorylinesToUnlockForSpecificOracleLevel(i);
    const storylineText = storylines
      .map((storyline) => storyline.getName())
      .join(t(", "));
    storylineBonusesLines.push(
      t(`* Storyline Oracle level {{i}}: **{{storylineText}}**`, {
        i: formatNumber(i),
        storylineText,
      }),
    );
  }
  const storylineBonusesText = storylineBonusesLines.join(t("\n"));

  return (
    <View>
      <View>{explanationBase}</View>
      <View>
        <Markdown>
          {t(
            `Your Storyline Oracle level will be {{level}}. This means you will begin the new World with the following Storylines already completed and their bonus active:
      
{{storylineBonusesText}}`,
            { level: formatNumber(level), storylineBonusesText },
          )}
        </Markdown>
      </View>
    </View>
  );
});

const BlazingSpeedDescription = React.memo((props: { level: number }) => {
  const level = props.level;

  const explanationBase = (
    <Markdown>
      {t(
        `The Blazing Speed Boon generally increases how fast you can make progress throughout the game. It affects exp and loot gain, as well as Channeling efficiency and item sale prices. This Boon can help make progress through a World significantly shorter.`,
      )}
    </Markdown>
  );

  if (level == 0) {
    return (
      <View>
        <View>{explanationBase}</View>
        <View>
          <Markdown>
            {t(`Your Blazing Speed level will be 0. You will get no benefits.`)}
          </Markdown>
        </View>
      </View>
    );
  }

  return (
    <View>
      <View>{explanationBase}</View>
      <View>
        <Markdown>
          {t(
            `Your Blazing Speed level will be {{level}}. You will get the following benefits:
    
* **{{expMultiplier}}** bonus to Element and Familiar exp gain;
* **{{channelingMultiplier}}** bonus to Channeling efficiency and item sale prices;
* **{{lootMultiplier}}** bonus to :coins: and :monstium: obtained from enemies, as well as to the chance of looting items.`,
            {
              level: formatNumber(level),
              expMultiplier: formatMultiplier(
                getNewGamePlusExpGainMultiplier(level),
              ),
              channelingMultiplier: formatMultiplier(
                getNewGamePlusChannelingAndSalePriceMultiplier(level),
              ),
              lootMultiplier: formatMultiplier(
                getNewGamePlusLootMultiplier(level),
              ),
            },
          )}
        </Markdown>
      </View>
    </View>
  );
});

const WorldDifficultyDescription = React.memo((props: { level: number }) => {
  const state = useGameState();
  const level = props.level;

  if (level == 0) {
    return (
      <View>
        <Markdown>
          {t(
            `Your World Difficulty level will be 0. It will be exactly the same as the first time you played through the game.`,
          )}
        </Markdown>
      </View>
    );
  }

  return (
    <View>
      <Markdown>
        {t(
          `Your World Difficulty level will be {{level}}.{{ultimateChallengeText}} Enemies will have the following bonuses:
    
* **{{hpMultiplier}}** bonus to Max:hp:;
* **{{attackMultiplier}}** bonus to :attack: and :defense:;
* **{{attackDelayMultiplier}}** bonus to :attackDelay:;
* **{{accuracyMultiplier}}** bonus to :accuracy: and :dodge:.

However, if you manage to clear the World, you will get a **{{boonPointMultiplier}}** bonus to the amount of Boon Points you will earn. This means you will get **{{boonPoints}}** Boon Points if you reach the ending "To Save a Life", and **{{doubleBoonPoints}}** Boon Points if you complete the Universal Challenge and reach the ending "Transcend Humanity".{{penUltimateChallengeRewardText}}`,
          {
            level: formatNumber(level),
            hpMultiplier: formatMultiplier(getNewGamePlusHpMultiplier(level)),
            attackMultiplier: formatMultiplier(
              getNewGamePlusOtherStatMultiplier(level),
            ),
            attackDelayMultiplier: formatMultiplier(
              getNewGamePlusAttackDelayMultiplier(level),
            ),
            accuracyMultiplier: formatMultiplier(
              getNewGamePlusAccuracyAndDodgeMultiplier(level),
            ),
            boonPointMultiplier: formatMultiplier(
              getNewGamePlusBoonPointsEarnedMultiplier(level),
            ),
            boonPoints: formatNumber(
              getNewGamePlusBoonPointsEarnedMultiplier(level) *
                BASE_BOON_POINTS_PER_WORLD,
            ),
            doubleBoonPoints: formatNumber(
              getNewGamePlusBoonPointsEarnedMultiplier(level) *
                BASE_BOON_POINTS_PER_WORLD *
                2,
            ),
            ultimateChallengeText:
              level > 10 ? t(" **This is the maximum World Difficulty!**") : "",
            penUltimateChallengeRewardText:
              level == 10 && getMaxPossibleDifficultyLevel(state) == 10
                ? t(
                    " **And if you clear the World, you may unlock something special...**",
                  )
                : "",
          },
        )}
      </Markdown>
    </View>
  );
});

function BoonRow(props: {
  level: number;
  title: string;
  decreasingPossible: boolean;
  increasingPossible: boolean;
  onPressMinus: () => void;
  onPressPlus: () => void;
}) {
  return (
    <View style={Styles.flexRowAlignCenter}>
      <View style={Styles.flex}>
        <Text style={Styles.bold}>{props.title}</Text>
        <Text>
          {props.level >= MAX_BOON_LEVEL
            ? t("Next level: -")
            : t("Next level: {{points}} pts", {
                points: formatNumber(
                  getIncrementalPointsRequiredForLevel(props.level + 1),
                ),
              })}
        </Text>
      </View>
      <View style={Styles.flexRowAlignCenter}>
        <Text>
          {t(`Lv{{level}} ({{points}} pts)`, {
            level: formatNumber(props.level),
            points: formatNumber(getTotalPointsRequiredForLevel(props.level)),
          })}
        </Text>
        <View style={Styles.ml16}>
          <PlusMinusAllocator
            disableMinus={!props.decreasingPossible}
            disablePlus={!props.increasingPossible}
            onPressMinus={props.onPressMinus}
            onPressPlus={props.onPressPlus}
          />
        </View>
      </View>
    </View>
  );
}

function useBoonManipulation(level, remainingPoints, setLevel) {
  const decreasingPossible = level > 0;
  const increasingPossible =
    level < MAX_BOON_LEVEL &&
    remainingPoints >= getIncrementalPointsRequiredForLevel(level + 1);

  const onPressMinus = useCallback(() => {
    if (decreasingPossible) {
      setLevel(level - 1);
    }
  }, [level, setLevel, decreasingPossible]);
  const onPressPlus = useCallback(() => {
    if (increasingPossible) {
      setLevel(level + 1);
    }
  }, [level, setLevel, increasingPossible]);

  return { decreasingPossible, increasingPossible, onPressMinus, onPressPlus };
}

export default function Worlds() {
  const state = useGameState();
  const theme = useTheme();

  const totalPointsAvailable = getTotalAvailableBoonPoints(state);
  const newBoonPointsAvailable = getNewBoonPointsAvailable(state);
  const maxDifficultyLevel = getMaxPossibleDifficultyLevel(state);

  const [sheerPower, setSheerPower] = useState(getSheerPowerLevel(state));
  const [storylineOracle, setStorylineOracle] = useState(
    getStorylineOracleLevel(state),
  );
  const [blazingSpeed, setBlazingSpeed] = useState(getBlazingSpeedLevel(state));
  const [worldDifficulty, setWorldDifficulty] = useState(
    getWorldDifficultyLevel(state),
  );

  const pointsRequiredForConfig = getPointsRequiredForConfiguration(
    sheerPower,
    storylineOracle,
    blazingSpeed,
  );
  const remainingPoints = totalPointsAvailable - pointsRequiredForConfig;

  const topMessage =
    t(`You now possess the ability to travel to other Worlds! You feel like, perhaps, you'd like a do-over of your grand adventure, but this time, in another World, a slightly different one. All the possibilities are laid bare before you...
  
By traveling a new world, **you will start over the game from the very beginning, and will forfeit everything:**

* Unlocked elements and Storylines will generally **not** carry over;
* Your MPL values, as well as your global element exp, production and loot multipliers, will **not** carry over.

However, you will continue having access to the Worlds screen - you will not need to unlock it again.`);

  const noContentWarning = (
    <Text style={[styles.error, { color: theme.colors.error }]}>
      {t(
        "Traveling to a different World does not unlock any new content! It is only meant as a way to experience the game again.",
      )}
    </Text>
  );

  const explanationForBoonsMessage = t(
    `Your prowess as a World traveler has granted you a total of **{{totalPointsAvailable}}** (**+{{newPointsAvailable}}**) Boon Points. You can use Boon Points to obtain powerful advantages across three different categories in the next World. Your Boon Points accumulate through Worlds, and you can reallocate them whenever traveling to a new World.`,
    {
      totalPointsAvailable: formatNumber(totalPointsAvailable),
      newPointsAvailable: formatNumber(newBoonPointsAvailable),
    },
  );

  const sheerPowerManipulation = useBoonManipulation(
    sheerPower,
    remainingPoints,
    setSheerPower,
  );
  const storylineOracleManipulation = useBoonManipulation(
    storylineOracle,
    remainingPoints,
    setStorylineOracle,
  );
  const blazingSpeedManipulation = useBoonManipulation(
    blazingSpeed,
    remainingPoints,
    setBlazingSpeed,
  );

  const difficultyExplanation = (
    <Markdown>
      {t(
        `Perhaps the adventure was too easy? For your next adventure, you can search for a World that is more difficult. They say that the higher the challenge, naturally, the higher the reward...
        
The maximum World Difficulty you can access is {{maxDifficulty}}.{{clearText}}`,
        {
          maxDifficulty: getMaxPossibleDifficultyLevel(state),
          clearText:
            getMaxPossibleDifficultyLevel(state) < 10
              ? t(
                  ' You can access higher difficulties by traveling to a new World and reaching the ending "To Save a Life".',
                )
              : "",
        },
      )}
    </Markdown>
  );

  const onPressDifficultyMinus = useCallback(() => {
    if (worldDifficulty > 0) {
      setWorldDifficulty(worldDifficulty - 1);
    }
  }, [worldDifficulty, setWorldDifficulty]);
  const onPressDifficultyPlus = useCallback(() => {
    if (worldDifficulty <= maxDifficultyLevel) {
      setWorldDifficulty(worldDifficulty + 1);
    }
  }, [worldDifficulty, setWorldDifficulty, maxDifficultyLevel]);

  const stateRef = useRef(state);
  stateRef.current = state;

  const [hasExportedGame, setHasExportedGame] = useState(false);
  const visible = useContext(VisibilityContext);
  useEffect(() => {
    if (!visible) {
      setHasExportedGame(false);
    }
  }, [visible]);

  const [copyToClipboardError, setCopyToClipboardError] = useState(null);

  const copyToClipboard = useCallback(async () => {
    const serializedData = await serializeSaveData(stateRef.current);
    await Clipboard.setStringAsync(serializedData);

    if (Platform.OS == "android" || Platform.OS == "ios") {
      const savedData = await Clipboard.getStringAsync();
      if (savedData != serializedData) {
        setCopyToClipboardError(
          t("Warning: save did not copy to clipboard correctly!"),
        );
        return;
      }
    }

    setHasExportedGame(true);
    setCopyToClipboardError(null);
  }, [setHasExportedGame]);

  const saveToFileCallback = useCallback(async () => {
    const result = await saveToFile(stateRef.current);
    setHasExportedGame(true);
  }, [setHasExportedGame]);

  const saveFileCategory = useMemo(
    () => (
      <View>
        <View>
          <Text>
            {t(
              "Since traveling to a new World is irreversible, you will need to export your save data first before being able to use your ability.",
            )}
          </Text>
        </View>
        <View style={Styles.mt8}>
          <Button onPress={copyToClipboard}>
            {t("Copy savefile to clipboard")}
          </Button>
        </View>
        {SAVE_TO_FILE_SUPPORTED && (
          <View style={Styles.mt4}>
            <Button onPress={saveToFileCallback}>{t("Save to File")}</Button>
          </View>
        )}
        {copyToClipboardError != null && (
          <View style={Styles.mt8}>
            <Text style={{ color: theme.colors.error }}>
              {copyToClipboardError}
            </Text>
          </View>
        )}
      </View>
    ),
    [
      copyToClipboard,
      SAVE_TO_FILE_SUPPORTED,
      saveToFileCallback,
      copyToClipboardError,
      theme,
    ],
  );

  const doNewGamePlus = useCallback(() => {
    stateRef.current.apply((state) => {
      return triggerNewGamePlus(
        state,
        sheerPower,
        storylineOracle,
        blazingSpeed,
        worldDifficulty,
      );
    });
  }, [sheerPower, storylineOracle, blazingSpeed, worldDifficulty]);

  return (
    <View style={styles.container}>
      <Text h1>{t("Worlds")}</Text>
      <View style={Styles.mt16}>
        <Markdown>{topMessage}</Markdown>
        {noContentWarning}
      </View>
      <UtilityCard style={Styles.mt16}>
        <Card.Title title={t("Boons")} />
        <Card.Content>
          <Markdown>{explanationForBoonsMessage}</Markdown>
          <View
            style={[
              Styles.mt16,
              Styles.p8,
              { backgroundColor: color(theme.colors.primary).alpha(0.1) },
            ]}
          >
            <BoonRow
              decreasingPossible={sheerPowerManipulation.decreasingPossible}
              increasingPossible={sheerPowerManipulation.increasingPossible}
              level={sheerPower}
              title={t("Sheer Power")}
              onPressMinus={sheerPowerManipulation.onPressMinus}
              onPressPlus={sheerPowerManipulation.onPressPlus}
            />
            <View style={Styles.mt8}>
              <SheerPowerDescription level={sheerPower} />
            </View>
          </View>
          <View style={Styles.p8}>
            <BoonRow
              decreasingPossible={
                storylineOracleManipulation.decreasingPossible
              }
              increasingPossible={
                storylineOracleManipulation.increasingPossible
              }
              level={storylineOracle}
              title={t("Storyline Oracle")}
              onPressMinus={storylineOracleManipulation.onPressMinus}
              onPressPlus={storylineOracleManipulation.onPressPlus}
            />
            <View style={Styles.mt8}>
              <StorylineOracleDescription level={storylineOracle} />
            </View>
          </View>
          <View
            style={[
              Styles.p8,
              { backgroundColor: color(theme.colors.primary).alpha(0.1) },
            ]}
          >
            <BoonRow
              decreasingPossible={blazingSpeedManipulation.decreasingPossible}
              increasingPossible={blazingSpeedManipulation.increasingPossible}
              level={blazingSpeed}
              title={t("Blazing Speed")}
              onPressMinus={blazingSpeedManipulation.onPressMinus}
              onPressPlus={blazingSpeedManipulation.onPressPlus}
            />
            <View style={Styles.mt8}>
              <BlazingSpeedDescription level={blazingSpeed} />
            </View>
          </View>
        </Card.Content>
      </UtilityCard>
      <UtilityCard style={Styles.mt16}>
        <Card.Title title={t("Difficulty")} />
        <Card.Content>
          {difficultyExplanation}
          <View style={Styles.mt16}>
            <View style={Styles.flexRowAlignCenter}>
              <View style={Styles.flex}>
                <Text style={Styles.bold}>{t("World Difficulty")}</Text>
              </View>
              <View style={Styles.flexRowAlignCenter}>
                <Text>
                  {t(`Lv{{level}}`, {
                    level: formatNumber(worldDifficulty),
                  })}
                </Text>
                <View style={Styles.ml16}>
                  <PlusMinusAllocator
                    disableMinus={worldDifficulty <= 0}
                    disablePlus={worldDifficulty >= maxDifficultyLevel}
                    onPressMinus={onPressDifficultyMinus}
                    onPressPlus={onPressDifficultyPlus}
                  />
                </View>
              </View>
            </View>
            <View style={Styles.mt8}>
              <WorldDifficultyDescription level={worldDifficulty} />
            </View>
          </View>
        </Card.Content>
      </UtilityCard>
      <View style={Styles.mt16}>{saveFileCategory}</View>
      <View style={Styles.mt16}>
        <Button
          buttonColor={theme.colors.errorContainer}
          textColor={theme.colors.onErrorContainer}
          onPress={doNewGamePlus}
          disabled={!hasExportedGame}
        >
          {t("Travel to a new World")}
        </Button>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  explanation: {
    marginTop: 16,
  },
  buttonContainer: {
    marginTop: 16,
  },
  warning: {
    marginTop: 8,
  },
  error: {
    fontWeight: "bold",
  },
});
