import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class StatusImmunity extends TemporaryEffect {
  getId(): string {
    return "statusImmunity";
  }
  getDisplayName(): string {
    return t("Status Immunity");
  }
  getIcon() {
    return require("./statusImmunity.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Immune to all debuffs");
  }
}

export const statusImmunity = new StatusImmunity();

export function loadStatusImmunity() {
  TemporaryEffects.register(statusImmunity);

  registerTemporaryEffectListener(
    "statusImmunityDebuffPrevention",
    (state, temporaryEffect) => {
      if (
        !hasTemporaryEffect(
          state,
          statusImmunity.getId(),
          temporaryEffect.target,
        )
      ) {
        return temporaryEffect;
      }
      const effect = TemporaryEffects.getById(temporaryEffect.id);
      if (effect.isBeneficial()) {
        return temporaryEffect;
      }
      // Prevent!
      temporaryEffect.endTime = getSecondsPlayed(state) - 1.0;
      return temporaryEffect;
    },
  );
}
