import { Entypo } from "@expo/vector-icons";
import color from "color";
import moment from "moment";
import React, { useCallback, useContext, useMemo, useRef } from "react";
import { StyleSheet, TouchableWithoutFeedback, View } from "react-native";
import { Card, useTheme } from "react-native-paper";
import { clearAllCaches } from "../backend/Caching";
import {
  ResearcherAutoAssignStrategy,
  createHypotheticalState,
} from "../backend/GameState";
import { ActionArea } from "../backend/action/ActionAreas";
import {
  TransmutationSpell,
  getVisibleTransmutationSpells,
} from "../backend/items/transmute/TransmutationSpell";
import { Ritual } from "../backend/rituals/Ritual";
import { getVisibleRituals } from "../backend/rituals/Rituals";
import {
  SpellElement,
  getElementExp,
  getElementLevel,
  getMaxElementLevel,
  getPrimaryElements,
  getTotalExpRequiredForLevel,
  getUnlockedElements,
  translateElement,
} from "../backend/spells/Elements";
import { Spell } from "../backend/spells/Spell";
import { getVisibleSpellsForElement } from "../backend/spells/Spells";
import { translateResearcherAutoAssignStrategy } from "../backend/study/ResearcherAutoAssignStrategy";
import {
  allocateResearchersToElement,
  explainBaseResearchRate,
  getBaseResearchRate,
  getFreeResearchers,
  getResearcherExpPerSecForElements,
  getTotalResearchers,
} from "../backend/study/Study";
import { keysOf, valuesOfCached } from "../utils/CoreUtils";
import { formatNumber, formatTimeLong } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import ActionsContainer from "./utility/ActionsContainer";
import { Card as UtilityCard } from "./utility/Card";
import { CheckBox } from "./utility/CheckBox";
import { Markdown } from "./utility/Markdown";
import { Picker } from "./utility/Picker";
import { PlusMinusAllocator } from "./utility/PlusMinusAllocator";
import { useExpandedUIFromContext } from "./utility/StatePersistedExpandedUI";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { UIContext, useUIStateFromContext } from "./utility/UIContext";
import Tooltip from "./utility/tooltip/PaperTooltip";
import { useGameState } from "./utility/useGameState";

function StudyRowDetails(props: { element: SpellElement }) {
  const element = props.element;
  const state = useGameState();

  const allElementLevels = getUnlockedElements(state).map((eachElement) =>
    getElementLevel(state, eachElement),
  );

  const researchEstimate = getResearcherExpPerSecForElements(state);

  const newSpellsText = useMemo(() => {
    // Avoid weird caching issues...
    state.temp.spellRequirementsCache = {};
    const thisLevelSpells = getVisibleSpellsForElement(
      state,
      element,
      ActionArea.SPELL_MENU,
    );
    const thisLevelCrafts = getVisibleTransmutationSpells(state);
    const thisLevelEmpowerments = thisLevelSpells.filter((spell) =>
      spell.isEmpoweringPossible(state),
    );
    const thisLevelRituals = getVisibleRituals(state);
    const currentElementLevel = getElementLevel(state, element);
    let nextUnlockLevelAdd = 0;
    let foundNewSpellsOrItems = false;
    let extraSpells: Spell[] = [];
    let extraCrafts: Spell[] = [];
    let extraEmpowerments: Spell[] = [];
    let extraRituals: Ritual[] = [];
    let fakeState = createHypotheticalState(state);
    while (
      !foundNewSpellsOrItems &&
      currentElementLevel + nextUnlockLevelAdd < getMaxElementLevel(state)
    ) {
      nextUnlockLevelAdd += 1;
      fakeState.run.elementExperience[element] = getTotalExpRequiredForLevel(
        fakeState,
        getElementLevel(state, element) + nextUnlockLevelAdd,
        element,
      );
      fakeState = clearAllCaches(fakeState);
      const nextLevelSpells = getVisibleSpellsForElement(
        fakeState,
        element,
        ActionArea.SPELL_MENU,
      );
      const nextLevelTransmutations = getVisibleTransmutationSpells(fakeState);
      const nextLevelEmpowerments = nextLevelSpells.filter((spell) =>
        spell.isEmpoweringPossible(fakeState),
      );
      const nextLevelRituals = getVisibleRituals(fakeState);
      extraSpells = nextLevelSpells.filter(
        (spell) => !thisLevelSpells.includes(spell),
      );
      extraCrafts = nextLevelTransmutations.filter(
        (item) => !thisLevelCrafts.includes(item),
      );
      extraEmpowerments = nextLevelEmpowerments.filter(
        (spell) => !thisLevelEmpowerments.includes(spell),
      );
      extraRituals = nextLevelRituals.filter(
        (ritual) => !thisLevelRituals.includes(ritual),
      );
      if (
        extraSpells.length > 0 ||
        extraCrafts.length > 0 ||
        extraEmpowerments.length > 0 ||
        extraRituals.length > 0
      ) {
        foundNewSpellsOrItems = true;
      }
    }
    const newSpells = extraSpells.map(
      (spell) => `*${spell.getBasicDisplayName(state)}*`,
    );
    const newTransmutations = extraCrafts.map((spell: TransmutationSpell) =>
      t("{{itemIcon}} *{{itemName}}*", {
        itemIcon: `:${spell.getItem().getId()}:`,
        itemName: spell.getItem().getName(spell.getItem().getDefaultParams()),
      }),
    );
    const newEmpowerments = extraEmpowerments.map(
      (spell) => `*${spell.getBasicDisplayName(state)}+*`,
    );
    const newRituals = extraRituals.map(
      (ritual) => `*${ritual.getBaseName()}*`,
    );

    const newUnlocks = [
      ...newSpells,
      ...newEmpowerments,
      ...newTransmutations,
      ...newRituals,
    ];

    return newUnlocks.length > 0
      ? t(`Your next unlock is at level {{level}}: {{unlocks}}`, {
          level: nextUnlockLevelAdd + currentElementLevel,
          unlocks: newUnlocks.join(t(", ")),
        })
      : t(
          `If you only level up this element, you will not unlock anything new!`,
        );
  }, [JSON.stringify(allElementLevels)]);

  const nextLevelRequiredExp =
    getTotalExpRequiredForLevel(
      state,
      getElementLevel(state, element) + 1,
      element,
    ) - getElementExp(state, element);

  return (
    <View style={Styles.p8}>
      <View>
        <Text>
          {t("To Next Level: {{exp}} Exp ({{time}})", {
            exp: formatNumber(nextLevelRequiredExp),
            time:
              (researchEstimate?.[element] ?? 0) == 0 ||
              nextLevelRequiredExp == Infinity
                ? ""
                : formatTimeLong(
                    moment.duration(
                      (1000 * nextLevelRequiredExp) /
                        (researchEstimate?.[element] ?? 0),
                    ),
                  ),
          })}
        </Text>
      </View>
      <View style={Styles.mt4}>
        <Markdown>{newSpellsText}</Markdown>
      </View>
    </View>
  );
}

function StudyRow(props: {
  element: SpellElement;
  applyAll: boolean;
  isOdd: boolean;
}) {
  const element = props.element;
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const [expanded, setExpanded] = useExpandedUIFromContext(
    state,
    "study:" + element,
    false,
  );
  const toggleExpanded = useCallback(() => setExpanded(!expanded), [expanded]);
  const freeResearchers = getFreeResearchers(state);
  const hasFreeResearchers = freeResearchers > 0;

  const current = state.run.studyAllocation?.[element] || 0;
  const onPressMinus = useCallback(
    () =>
      stateRef.current.apply(
        allocateResearchersToElement(element, props.applyAll ? -Infinity : -1),
      ),
    [element, props.applyAll],
  );
  const onPressPlus = useCallback(
    () =>
      stateRef.current.apply(
        allocateResearchersToElement(element, props.applyAll ? Infinity : 1),
      ),
    [element, props.applyAll],
  );
  const elementLevel = getElementLevel(state, element);

  const theme = useTheme();

  const elementText = useMemo(
    () => (
      <Text numberOfLines={3}>
        {t("{{element}}{{elementPrimaryText}}: Lv{{level}}", {
          element: translateElement(element),
          elementPrimaryText: getPrimaryElements(state).includes(element)
            ? t(" (Primary)")
            : "",
          level: formatNumber(elementLevel),
        })}
      </Text>
    ),
    [element, getPrimaryElements(state).join(), elementLevel],
  );
  return useMemo(
    () => (
      <View
        key={element}
        style={[
          styles.singleElementContainer,
          props.isOdd && {
            backgroundColor: color(theme.colors.primary).alpha(0.1),
          },
        ]}
      >
        <View style={styles.row}>
          <View style={styles.elementTextContainer}>{elementText}</View>
          <View style={[styles.row, styles.rightHandSide]}>
            <Text>{formatNumber(current)}</Text>
            <View style={styles.plusMinusAllocatorContainer}>
              <PlusMinusAllocator
                disableMinus={current === 0}
                disablePlus={!hasFreeResearchers}
                onPressMinus={onPressMinus}
                onPressPlus={onPressPlus}
              />
            </View>
            <TouchableWithoutFeedback
              onPress={toggleExpanded}
              accessibilityRole="header"
              accessibilityLabel={expanded ? t("Collapse") : t("Expand")}
            >
              <View style={styles.expansionContainer}>
                <Entypo
                  name={expanded ? "chevron-down" : "chevron-right"}
                  size={24}
                  color={theme.colors.onBackground}
                />
              </View>
            </TouchableWithoutFeedback>
          </View>
        </View>
        {expanded && (
          <View>
            <StudyRowDetails element={element} />
          </View>
        )}
      </View>
    ),
    [
      elementText,
      current,
      hasFreeResearchers,
      onPressMinus,
      onPressPlus,
      theme,
      props.isOdd,
      expanded,
      toggleExpanded,
    ],
  );
}

export function Study() {
  const state = useGameState();
  const uiContext = useContext(UIContext);
  const stateRef = useRef(state);
  stateRef.current = state;
  const freeResearchers = getFreeResearchers(state);
  const totalResearchers = getTotalResearchers(state);

  const [applyAll, setApplyAll] = useUIStateFromContext(
    uiContext,
    "research_applyall",
    false,
  );

  const toggleApplyAll = useCallback(() => {
    setApplyAll(!applyAll);
  }, [applyAll, setApplyAll]);

  const autoAssign = state.options.researcherAutoAssignStrategy;
  const setAutoAssign = useCallback((value: ResearcherAutoAssignStrategy) => {
    stateRef.current.apply((s) => {
      s.options.researcherAutoAssignStrategy = value;
      return s;
    });
  }, []);

  const autoAssignPicker = useMemo(() => {
    const items = valuesOfCached(ResearcherAutoAssignStrategy).map((value) => ({
      key: value,
      value,
      label: translateResearcherAutoAssignStrategy(value),
    }));
    return (
      <Picker
        onValueChange={setAutoAssign}
        selectedValue={autoAssign}
        disablePlaceholder={true}
        data={items}
        style={{ maxWidth: 120 }}
        maxLength={16}
      />
    );
  }, [autoAssign, setAutoAssign]);

  const rateExplanation = explainBaseResearchRate(state);
  const expRateContents = t("{{exp}} / sec", {
    exp: formatNumber(getBaseResearchRate(state), {
      showDecimals: true,
    }),
  });

  const bottomSection = useMemo(
    () => (
      <View>
        <View style={styles.assignAllContainer}>
          <CheckBox
            title={t("Assign / unassign all at once")}
            checked={applyAll}
            onPress={toggleApplyAll}
          />
        </View>
        <View style={[Styles.mt4, Styles.ph16, Styles.flexRowSpaceBetween]}>
          <Text>{t("Auto-assign researchers: ")}</Text>
          <View style={Styles.ml16}>{autoAssignPicker}</View>
        </View>
        <View style={[styles.row, styles.researcherCapacityContainer]}>
          <Text>{t("Researchers available: ")}</Text>
          <Text>
            {t("{{free}}/{{total}}", {
              free: formatNumber(freeResearchers),
              total: formatNumber(totalResearchers),
            })}
          </Text>
        </View>
        <View style={[styles.row, styles.researcherCapacityContainer]}>
          <Text>{t("Exp per researcher: ")}</Text>
          {rateExplanation != null ? (
            <Tooltip title={rateExplanation}>
              <Text style={Styles.underlined}>{expRateContents}</Text>
            </Tooltip>
          ) : (
            <Text>{expRateContents}</Text>
          )}
        </View>
        <View>
          <ActionsContainer area={ActionArea.STUDY} />
        </View>
      </View>
    ),
    [
      expRateContents,
      rateExplanation,
      totalResearchers,
      freeResearchers,
      applyAll,
      toggleApplyAll,
      autoAssignPicker,
    ],
  );

  const elements = getUnlockedElements(state);

  const disableAllMinus = freeResearchers === totalResearchers;
  const disableAllPlus = freeResearchers <= 0;
  const onPressMinusAll = useCallback(() => {
    stateRef.current.apply((state) => {
      for (let element of keysOf(state.run.studyAllocation)) {
        allocateResearchersToElement(element, applyAll ? -Infinity : -1)(state);
      }
      return state;
    });
  }, [applyAll]);
  const onPressPlusAll = useCallback(() => {
    stateRef.current.apply((state) => {
      do {
        for (let element of getUnlockedElements(state)) {
          allocateResearchersToElement(element, 1)(state);
        }
      } while (applyAll && getFreeResearchers(state) > 0);
      return state;
    });
  }, [applyAll]);

  const theme = useTheme();

  return useMemo(() => {
    return (
      <View>
        <UtilityCard>
          <Card.Title title={t("Elements")} accessibilityRole="header" />
          <View style={[Styles.ph8, Styles.pb8]}>
            {elements.map((element, index) => {
              return (
                <StudyRow
                  key={element}
                  element={element}
                  applyAll={applyAll}
                  isOdd={index % 2 != 0}
                />
              );
            })}
            <View
              key="All"
              style={[
                styles.row,
                styles.singleElementContainer,
                elements.length % 2 != 0 && {
                  backgroundColor: color(theme.colors.primary).alpha(0.1),
                },
              ]}
            >
              <View style={styles.elementTextContainer}>
                <Text numberOfLines={3}>{t("Assign to every Element")}</Text>
              </View>
              <View style={[styles.row, styles.rightHandSide]}>
                <View style={styles.plusMinusAllocatorContainer}>
                  <PlusMinusAllocator
                    disableMinus={disableAllMinus}
                    disablePlus={disableAllPlus}
                    onPressMinus={onPressMinusAll}
                    onPressPlus={onPressPlusAll}
                  />
                </View>
              </View>
              <View style={styles.expansionPlaceholder} />
            </View>
          </View>
        </UtilityCard>
        {bottomSection}
      </View>
    );
  }, [
    elements.length,
    bottomSection,
    applyAll,
    disableAllMinus,
    disableAllPlus,
    onPressMinusAll,
    onPressPlusAll,
    theme,
  ]);
}

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  plusMinusAllocatorContainer: {
    marginLeft: 16,
  },
  assignAllContainer: {
    marginTop: 8,
    paddingHorizontal: 16,
  },
  researcherCapacityContainer: {
    marginTop: 4,
    paddingHorizontal: 16,
  },
  singleElementContainer: {
    padding: 4,
  },
  checkboxContainer: {
    marginLeft: 0,
    marginTop: 8,
    padding: 0,
  },
  checkboxText: {
    fontWeight: "normal",
  },
  rightHandSide: {
    marginLeft: 16,
  },
  elementTextContainer: {
    flex: 1,
  },
  valueText: {
    minWidth: 60,
    textAlign: "right",
    marginLeft: 8,
  },
  expansionContainer: {
    // @ts-ignore "pointer" is allowed here
    cursor: "pointer",
  },
  expansionPlaceholder: {
    width: 24,
  },
});
