import { valuesOf } from "../../../../utils/CoreUtils";
import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Resource } from "../../../Resources";
import { RESOURCE_FOR_ELEMENT } from "../../../spells/channeling/ChannelingResources";
import { SpellElement, translateElement } from "../../../spells/Elements";
import { Item, ItemParams } from "../../Item";
import { ItemTagEnum } from "../../ItemTagEnum";
import {
  registerTransmutationSpellAndItem,
  TransmutationSpell,
} from "../TransmutationSpell";

class ElementalPrism extends Item {
  element: SpellElement;
  icon: any;
  baseSalePrice: number;

  constructor(element: SpellElement, icon: any, baseSalePrice: number) {
    super();
    this.element = element;
    this.icon = icon;
    this.baseSalePrice = baseSalePrice;
  }

  getId(): string {
    return "elementalPrism" + this.element;
  }

  getTags(): ItemTagEnum[] {
    return [ItemTagEnum.Material];
  }

  getPicture() {
    return this.icon;
  }

  getElement(): SpellElement {
    return this.element;
  }

  getBaseName(params: ItemParams): string {
    return t("{{element}} Prism", { element: translateElement(this.element) });
  }

  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A prism built with very-high-density {{resourceIcon}}. Useful for more advanced transmutation.",
      { resourceIcon: `:${RESOURCE_FOR_ELEMENT[this.element].toLowerCase()}:` },
    );
  }

  getEffect(state: GameState, params: ItemParams): string | undefined {
    return undefined;
  }

  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return this.baseSalePrice;
  }
}

class ElementalPrismTransmutation extends TransmutationSpell {
  item: ElementalPrism;
  essenceAmountRequired: number;

  constructor(item: ElementalPrism, essenceAmountRequired: number) {
    super();
    this.item = item;
    this.essenceAmountRequired = essenceAmountRequired;
  }

  getItem(): Item {
    return this.item;
  }

  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    const requirements = {};
    requirements[this.item.getElement()] = 24;
    return requirements;
  }

  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    const resources = { Mana: 200 };
    resources[RESOURCE_FOR_ELEMENT[this.item.getElement()]] =
      this.essenceAmountRequired;
    return {
      resources,
      items: {},
    };
  }

  getBaseAutocastPointsRequirement(): number {
    return 30;
  }

  getElement(): SpellElement {
    return this.item.getElement();
  }
}

export function loadElementalPrisms() {
  const perElementData: Record<SpellElement, any> = {
    Fire: {
      icon: require("./firePrism.png"),
      salePriceMultiplier: 1.5,
    },
    Earth: {
      icon: require("./earthPrism.png"),
      salePriceMultiplier: 1.5,
    },
    Water: {
      icon: require("./waterPrism.png"),
      salePriceMultiplier: 1.0,
    },
    Air: {
      icon: require("./airPrism.png"),
      salePriceMultiplier: 1.0,
    },
    Poison: {
      icon: require("./poisonPrism.png"),
      salePriceMultiplier: 3.0,
    },
    Mind: {
      icon: require("./mindPrism.png"),
      salePriceMultiplier: 2.0,
    },
    Life: {
      icon: require("./lifePrism.png"),
      salePriceMultiplier: 2.5,
    },
    Electric: {
      icon: require("./electricPrism.png"),
      salePriceMultiplier: 10.0,
    },
    Death: {
      icon: require("./deathPrism.png"),
      salePriceMultiplier: 5.0,
    },
    Holy: {
      icon: require("./holyPrism.png"),
      salePriceMultiplier: 6.0,
    },
    Time: {
      icon: require("./timePrism.png"),
      salePriceMultiplier: 1.5,
    },
    Space: {
      icon: require("./spacePrism.png"),
      salePriceMultiplier: 2.0,
    },
  };
  const basePrice = 2000;
  const baseEssenceAmount = 50000;
  for (let element of valuesOf(SpellElement)) {
    const perElement = perElementData[element];
    const prismItem = new ElementalPrism(
      element,
      perElement.icon,
      basePrice * perElement.salePriceMultiplier,
    );
    const transmuteSpell = new ElementalPrismTransmutation(
      prismItem,
      baseEssenceAmount,
    );
    registerTransmutationSpellAndItem(transmuteSpell);
  }
}
