import memoize from "sonic-memoize";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { hasWorldFlag } from "../../Flags";
import { GameState, GameStateTransform } from "../../GameState";
import { Resource, grantResource } from "../../Resources";
import {
  Familiars,
  getCurrentCompanionData,
  getFamiliarData,
  pushToFamiliarMessageLog,
  wouldBeThrottledIfPushingToCreatureMessageLog,
} from "../../familiars/Familiar";
import {
  getCurrentCompanionClass,
  getCurrentCompanionId,
} from "../../familiars/FamiliarUtils";
import { Identifiable } from "../../generic/Identifiable";
import { addToInventory } from "../../items/Inventory";
import { Items } from "../../items/Items";
import {
  applyTransformations,
  applyTransformationsCached,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { AttackTarget } from "../AttackTarget";
import { CombatStat, getCachedCombatStat } from "../CombatStats";
import {
  getCumulativeLosses,
  pushToMessageLog,
  standardEnemyAttackEffect,
} from "../Exploration";
import { hasCurrentActiveCompanion } from "../Summoning";
import { Enemies } from "./Enemies";

export interface BattlerStats {
  maxHP: number;
  attack: number;
  attackDelay: number;
  defense: number;
  accuracy: number;
  dodge: number;
  crit: number;
}

export interface BattlerAction {
  name: string;
  delay: number;
  transform: GameStateTransform;
}

export interface EnemyLoot {
  itemId: string;
  chance: number;
  amount: number;
  params?: any;
}

export enum BestiaryState {
  Hidden = "Hidden",
  SpriteOnly = "SpriteOnly",
  Basic = "Basic",
  Expanded = "Expanded",
  Loot = "Loot",
}

export function getCombatStatFromBattlerStat(battlerStat: string): CombatStat {
  switch (battlerStat) {
    case "maxHP":
      return CombatStat.MaxHP;
    case "attack":
      return CombatStat.Attack;
    case "attackDelay":
      return CombatStat.AttackDelay;
    case "defense":
      return CombatStat.Defense;
    case "accuracy":
      return CombatStat.Accuracy;
    case "dodge":
      return CombatStat.Dodge;
    case "crit":
      return CombatStat.Crit;
  }
  throw new Error("Invalid battler stat");
}

export function getCumulativeEnemyKills(
  state: GameState,
  enemyId: string,
): number {
  return (
    getCumulativeEnemyKillsFromCreatures(state, enemyId) +
    (state.world.timesEnemyDefeated?.[enemyId]?.player ?? 0)
  );
}

export function getCumulativeEnemyKillsFromCreatures(
  state: GameState,
  enemyId: string,
): number {
  return state.world.timesEnemyDefeated?.[enemyId]?.familiars ?? 0;
}

export function getBestiaryVisibleEnemies(state: GameState): Enemy[] {
  return Enemies.getAll().filter(
    (enemy) => enemy.getBestiaryState(state) != BestiaryState.Hidden,
  );
}

export function isBestiaryUnlocked(state: GameState): boolean {
  return hasWorldFlag(state, "storyline_bookOfMonsters_completed");
}

const enemyStatTags = memoize((stat) => [AttackTarget.Enemy, stat]);

export const KILLS_FOR_EXPANDED_BESTIARY = 10;
export const KILLS_FOR_BESTIARY_LOOT = 50;

export abstract class Enemy implements Identifiable {
  abstract getId(): string;
  abstract getName(): string;
  abstract getBaseStats(): BattlerStats;
  abstract getPicture(state: GameState): any;
  abstract getLevel(): number;

  getMaxHP(state: GameState): number {
    return getCachedCombatStat(
      state,
      AttackTarget.Enemy,
      CombatStat.MaxHP,
      (state) =>
        applyTransformations(
          enemyStatTags(CombatStat.MaxHP),
          state,
          this.getBaseStats().maxHP,
        ),
    );
  }
  getAttack(state: GameState): number {
    return getCachedCombatStat(
      state,
      AttackTarget.Enemy,
      CombatStat.Attack,
      (state) =>
        applyTransformations(
          enemyStatTags(CombatStat.Attack),
          state,
          this.getBaseStats().attack,
        ),
    );
  }
  getAttackDelay(state: GameState): number {
    // Transformed elsewhere
    return this.getBaseStats().attackDelay;
  }
  getCriticalPoints(state: GameState): number {
    return getCachedCombatStat(
      state,
      AttackTarget.Enemy,
      CombatStat.Crit,
      (state) =>
        applyTransformations(
          enemyStatTags(CombatStat.Crit),
          state,
          this.getBaseStats().crit,
        ),
    );
  }
  getDefense(state: GameState): number {
    return getCachedCombatStat(
      state,
      AttackTarget.Enemy,
      CombatStat.Defense,
      (state) =>
        applyTransformations(
          enemyStatTags(CombatStat.Defense),
          state,
          this.getBaseStats().defense,
        ),
    );
  }
  getAccuracyPoints(state: GameState): number {
    return getCachedCombatStat(
      state,
      AttackTarget.Enemy,
      CombatStat.Accuracy,
      (state) =>
        applyTransformations(
          enemyStatTags(CombatStat.Accuracy),
          state,
          this.getBaseStats().accuracy,
        ),
    );
  }
  getDodgePoints(state: GameState): number {
    return getCachedCombatStat(
      state,
      AttackTarget.Enemy,
      CombatStat.Dodge,
      (state) =>
        applyTransformations(
          enemyStatTags(CombatStat.Dodge),
          state,
          this.getBaseStats().dodge,
        ),
    );
  }

  getNextAction(state: GameState): BattlerAction {
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  abstract getCoinsAwardedBase(state: GameState): number;

  abstract getMonstiumAwardedBase(state: GameState): number;

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [];
  }

  isBoss(): boolean {
    return false;
  }

  getBackdropColor(state: GameState): string | undefined {
    return undefined;
  }

  getBestiaryState(state: GameState): BestiaryState {
    const kills = getCumulativeEnemyKills(state, this.getId());
    const losses = getCumulativeLosses(state, this.getId());
    if (kills >= (this.isBoss() ? 1 : KILLS_FOR_BESTIARY_LOOT)) {
      return BestiaryState.Loot;
    }
    if (kills >= (this.isBoss() ? 1 : KILLS_FOR_EXPANDED_BESTIARY)) {
      return BestiaryState.Expanded;
    }
    if (kills > 0 || losses >= 2) {
      return BestiaryState.Basic;
    }
    if (losses > 0) {
      return BestiaryState.SpriteOnly;
    }
    return BestiaryState.Hidden;
  }

  lootTags = memoize((isFromCreatures, resource) => [
    TransformationTags.Loot,
    resource,
    isFromCreatures
      ? TransformationTags.FromFamiliarExploration
      : TransformationTags.FromExploration,
  ]);

  lootChanceTags = memoize((isFromCreatures) => [
    TransformationTags.LootChance,
    isFromCreatures
      ? TransformationTags.FromFamiliarExploration
      : TransformationTags.FromExploration,
  ]);

  onEncounter(state: GameState): GameState {
    return state;
  }

  onPlayerDeath(state: GameState): GameState {
    return state;
  }

  _enemyIdParams = { enemyId: this.getId() };

  _familiarParams = memoize((familiarId: string) => ({
    ...this._enemyIdParams,
    familiarId,
  }));

  onDeath(
    state: GameState,
    isFromFamiliars: boolean,
    familiarId?: string,
  ): GameState {
    const totalTimesDefeated = state.world.timesEnemyDefeated?.[
      this.getId()
    ] ?? { player: 0, familiars: 0, losses: 0 };
    if (isFromFamiliars) {
      totalTimesDefeated.familiars++;
    } else {
      totalTimesDefeated.player++;
    }
    state.world.timesEnemyDefeated[this.getId()] = totalTimesDefeated;

    const baseCoinsAwarded = this.getCoinsAwardedBase(state);
    const baseMonstiumAwarded = this.getMonstiumAwardedBase(state);
    const coinsAwarded = applyTransformationsCached(
      this.lootTags(isFromFamiliars, Resource.Coins),
      state,
      baseCoinsAwarded,
    );
    const monstiumAwarded = applyTransformationsCached(
      this.lootTags(isFromFamiliars, Resource.Monstium),
      state,
      baseMonstiumAwarded,
    );
    const familiarExpAwarded = applyTransformationsCached(
      this.lootTags(isFromFamiliars, TransformationTags.FamiliarExp),
      state,
      isFromFamiliars ? baseMonstiumAwarded / 2 : baseMonstiumAwarded,
      this._familiarParams(
        isFromFamiliars ? familiarId : getCurrentCompanionId(state),
      ),
    );

    state = grantResource(Resource.Coins, coinsAwarded)(state);
    state = grantResource(Resource.Monstium, monstiumAwarded)(state);
    if (!isFromFamiliars) {
      state = pushToMessageLog(
        state,
        t(
          `You found {{coinsAwarded}} :coins: and {{monstiumAwarded}} :monstium:!`,
          {
            coinsAwarded: formatNumber(coinsAwarded),
            monstiumAwarded: formatNumber(monstiumAwarded),
          },
        ),
      );
      if (hasCurrentActiveCompanion(state)) {
        const familiar = getCurrentCompanionData(state);
        const familiarClass = getCurrentCompanionClass(state);
        const oldLevel = familiarClass.getLevel(state, familiar.id);
        familiar.exp += familiarExpAwarded;
        const newLevel = familiarClass.getLevel(state, familiar.id);
        if (newLevel > oldLevel) {
          state = pushToMessageLog(
            state,
            t(
              `Your familiar earned {{expAwarded}} exp and leveled up to {{newLevel}}!`,
              {
                expAwarded: formatNumber(familiarExpAwarded),
                newLevel: formatNumber(newLevel),
              },
            ),
          );
          state = pushToFamiliarMessageLog(
            state,
            familiar.id,
            t(
              `Your familiar earned {{expAwarded}} exp and leveled up to {{newLevel}}!`,
              {
                expAwarded: formatNumber(familiarExpAwarded),
                newLevel: formatNumber(newLevel),
              },
            ),
          );
        } else {
          state = pushToMessageLog(
            state,
            t(`Your familiar earned {{expAwarded}} exp!`, {
              expAwarded: formatNumber(familiarExpAwarded),
            }),
          );
          state = pushToFamiliarMessageLog(
            state,
            familiar.id,
            t(`Your familiar earned {{expAwarded}} exp!`, {
              expAwarded: formatNumber(familiarExpAwarded),
            }),
          );
        }
      }
    } else {
      const familiar = getFamiliarData(state, familiarId);
      const familiarClass = Familiars.getById(familiar.familiarClassId);
      const oldLevel = familiarClass.getLevel(state, familiar.id);
      familiar.exp += familiarExpAwarded;
      const newLevel = familiarClass.getLevel(state, familiar.id);
      if (!wouldBeThrottledIfPushingToCreatureMessageLog(state, familiarId)) {
        state = pushToFamiliarMessageLog(
          state,
          familiarId,
          t(
            `Your familiar found {{coinsAwarded}} :coins: and {{monstiumAwarded}} :monstium:!`,
            {
              coinsAwarded: formatNumber(coinsAwarded),
              monstiumAwarded: formatNumber(monstiumAwarded),
            },
          ),
        );
        if (newLevel > oldLevel) {
          state = pushToFamiliarMessageLog(
            state,
            familiar.id,
            t(
              `Your familiar earned {{expAwarded}} exp and leveled up to {{newLevel}}!`,
              {
                expAwarded: formatNumber(familiarExpAwarded),
                newLevel: formatNumber(newLevel),
              },
            ),
          );
        } else {
          state = pushToFamiliarMessageLog(
            state,
            familiar.id,
            t(`Your familiar earned {{expAwarded}} exp!`, {
              expAwarded: formatNumber(familiarExpAwarded),
            }),
          );
        }
      }
    }
    const itemsAwarded = this.getItemsAwardedBase(state);
    itemsAwarded.forEach((enemyLoot) => {
      let chance = enemyLoot.chance;
      chance = applyTransformations(
        this.lootChanceTags(isFromFamiliars),
        state,
        chance,
      );
      if (Math.random() < chance) {
        const item = Items.getById(enemyLoot.itemId);
        const params = enemyLoot.params || {};
        const itemOccurrence = { itemId: enemyLoot.itemId, params };
        const amount = applyTransformations(
          this._memoizedEnemyLootTags(enemyLoot.itemId, isFromFamiliars),
          state,
          enemyLoot.amount,
        );
        state = addToInventory(itemOccurrence, amount, state);
        const foundLoot =
          state.world.knownEnemiesThatDrop[enemyLoot.itemId] ?? {};
        if (!foundLoot[this.getId()]) {
          foundLoot[this.getId()] = true;
        }
        state.world.knownEnemiesThatDrop[enemyLoot.itemId] = foundLoot;
        if (!isFromFamiliars) {
          state = pushToMessageLog(
            state,
            t("You found {{amount}} {{item}}!", {
              amount: amount,
              item: item.getName(params),
            }),
          );
        } else {
          state = pushToFamiliarMessageLog(
            state,
            familiarId,
            t("Your familiar found {{amount}} {{item}}!", {
              amount: amount,
              item: item.getName(params),
            }),
          );
        }
      }
    });
    return state;
  }

  _memoizedEnemyLootTags = memoize(
    (enemyLootId: string, isFromFamiliars: boolean): string[] => {
      return [
        TransformationTags.LootItemAmount,
        enemyLootId,
        this.getId(),
        isFromFamiliars
          ? TransformationTags.FromFamiliarExploration
          : TransformationTags.FromExploration,
      ];
    },
  );

  // Tips on how to fight
  getTips(): string | undefined {
    return undefined;
  }
}
