import mem from "mem";
import { formatNumber } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import { GameState, GameStateTransform } from "./GameState";
import { Action } from "./action/Action";
import { getActionById } from "./action/Actions";
import { getAmountOfItem, isItemOccurrenceEqual } from "./items/Inventory";
import type { Item } from "./items/Item";
import { ItemOccurrence } from "./items/Item";
import { ItemTagEnum } from "./items/ItemTagEnum";
import { Items } from "./items/Items";
import { ConsumableItem } from "./items/consumable/ConsumableItem";

export interface QuickbarItemData {
  type: "item";
  occurrence: ItemOccurrence;
}

export interface QuickbarActionData {
  type: "action";
  id: string;
}

export type QuickbarData = QuickbarItemData | QuickbarActionData;

export interface QuickbarAction {
  getDisplayName(state: GameState): string;
  isEnabled(state: GameState): boolean;
  isActive(state: GameState): boolean;
  getTransform(state: GameState): GameStateTransform;
  toQuickbarData(): QuickbarData;
}

class QuickbarItem {
  data: QuickbarItemData;

  constructor(data: QuickbarItemData) {
    this.data = data;
  }

  getItem(): Item {
    const itemOccurrence = this.data.occurrence;
    const item = Items.getById(itemOccurrence.itemId);
    return item;
  }

  isActive(state: GameState): boolean {
    const item = this.getItem();
    return item.isActive(state);
  }

  getDisplayName(state: GameState): string {
    const item = this.getItem();
    const name = item.getName(this.data.occurrence.params);
    const amount = getAmountOfItem(this.data.occurrence, state);
    let cooldown = 0;
    if (item.getTags().includes(ItemTagEnum.Consumable)) {
      cooldown = (item as ConsumableItem).getCooldownLeftSec(state);
    }
    return t("{{name}}{{amountText}}{{cooldownText}}", {
      name,
      amountText: amount != 1 ? t(" ({{amount}})", { amount }) : "",
      cooldownText:
        cooldown > 0
          ? t(" ({{cooldown}} sec)", {
              cooldown: formatNumber(cooldown, {
                showDecimals: false,
              }),
            })
          : "",
    });
  }

  isEnabled(state: GameState): boolean {
    return (
      this.getItem().getPrimaryAction(state, this.data.occurrence.params)
        ?.isEnabled || false
    );
  }

  getTransform(state: GameState): GameStateTransform {
    return (
      this.getItem().getPrimaryAction(state, this.data.occurrence.params)
        ?.transform || ((state) => state)
    );
  }

  toQuickbarData(): QuickbarData {
    return this.data;
  }
}

const doActionMemoized = mem((action: Action) => {
  return action.doAction.bind(action, {
    isAutomatic: false,
  });
});

export class QuickbarActionImpl {
  data: QuickbarActionData;

  constructor(data: QuickbarActionData) {
    this.data = data;
  }

  getAction(): Action {
    return getActionById(this.data.id);
  }

  isActive(state: GameState): boolean {
    return this.getAction().isActive(state);
  }

  getDisplayName(state: GameState): string {
    return this.getAction().getDisplayName(state);
  }

  isEnabled(state: GameState): boolean {
    return this.getAction().isEnabled(state);
  }

  getTransform(state: GameState): GameStateTransform {
    return doActionMemoized(this.getAction());
  }

  toQuickbarData(): QuickbarData {
    return this.data;
  }
}

function getCurrentQuickbar(state: GameState): QuickbarData[] {
  let currentQuickbar = state.world.quickbars?.[state.world.currentQuickbar];
  if (currentQuickbar == null) {
    currentQuickbar = [];
    state.world.quickbars[state.world.currentQuickbar] = currentQuickbar;
  }
  return currentQuickbar;
}

export function getQuickbarActions(state: GameState): QuickbarAction[] {
  return getCurrentQuickbar(state).map((quickbarData) => {
    if (quickbarData.type == "item") {
      return new QuickbarItem(quickbarData);
    } else {
      return new QuickbarActionImpl(quickbarData);
    }
  });
}

export function addActionToQuickbar(
  state: GameState,
  action: Action,
): GameState {
  return addToQuickbar(state, {
    type: "action",
    id: action.getId(),
  });
}

export function addItemToQuickbar(
  state: GameState,
  itemOccurrence: ItemOccurrence,
): GameState {
  return addToQuickbar(state, {
    type: "item",
    occurrence: itemOccurrence,
  });
}

export function addToQuickbar(state: GameState, data: QuickbarData): GameState {
  if (isInQuickbar(state, data)) {
    return state;
  }
  if (isQuickbarFull(state)) {
    return state;
  }
  const newQuickbar = getCurrentQuickbar(state);
  newQuickbar.push(data);
  return state;
}

export function removeFromQuickbar(
  state: GameState,
  data: QuickbarData,
): GameState {
  const stringifiedData = JSON.stringify(data);
  const index = getCurrentQuickbar(state).findIndex(
    (element) => JSON.stringify(element) == stringifiedData,
  );
  if (index < 0) {
    return state;
  }
  getCurrentQuickbar(state).splice(index, 1);
  return state;
}

export function isQuickbarFull(state: GameState): boolean {
  return getCurrentQuickbar(state).length >= 10;
}

export function isInQuickbar(state: GameState, data: QuickbarData): boolean {
  return (
    getCurrentQuickbar(state).findIndex(
      (element) =>
        (element.type == data.type &&
          data.type == "action" &&
          element.type == "action" &&
          data.id == element.id) ||
        (data.type == "item" &&
          element.type == "item" &&
          isItemOccurrenceEqual(data.occurrence, element.occurrence)),
    ) >= 0
  );
}
