import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import {
  CombatStat,
  calculatePlayerAccuracyPoints,
  calculatePlayerAttack,
  calculatePlayerAttackDelaySec,
  calculatePlayerCritPoints,
  calculatePlayerDefense,
  calculatePlayerDodgePoints,
  calculatePlayerMaxHP,
  explainPlayerCombatStat,
} from "../../backend/exploration/CombatStats";
import { getCurrentPlayerHP } from "../../backend/exploration/Exploration";
import { Enemy } from "../../backend/exploration/enemies/Enemy";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import Styles from "./Styles";
import { Text } from "./Text";
import { GameIcon } from "./icons/GameIcon";
import Tooltip from "./tooltip/PaperTooltip";
import { useGameState } from "./useGameState";

function CombatStatsRow(props: {
  stat: string;
  icon: string;
  value: string;
  valueMax?: string;
  explanation?: string;
  hideText?: boolean;
}) {
  if (props.explanation != null && props.explanation != "") {
    return (
      <View style={styles.inlineRow}>
        <View style={styles.inlineRow}>
          <GameIcon icon={props.icon} style={styles.icon} small />
          {!props.hideText && (
            <Text doNotScaleFonts={true}>
              {t("{{stat}}: ", { stat: props.stat })}
            </Text>
          )}
        </View>
        <Tooltip title={props.explanation}>
          {props.valueMax != null ? (
            <Text doNotScaleFonts={true}>
              <Text doNotScaleFonts={true}>
                {t("{{value}}/", { value: props.value })}
              </Text>
              <Text doNotScaleFonts={true} style={Styles.underlined}>
                {t("{{value}}", { value: props.valueMax })}
              </Text>
            </Text>
          ) : (
            <Text doNotScaleFonts={true} style={Styles.underlined}>
              {t("{{value}}", { value: props.value })}
            </Text>
          )}
        </Tooltip>
      </View>
    );
  }
  return (
    <View style={styles.inlineRow}>
      <View style={styles.inlineRow}>
        <GameIcon icon={props.icon} style={styles.icon} small />
        {!props.hideText && (
          <Text doNotScaleFonts={true}>
            {t("{{stat}}: ", { stat: props.stat })}
          </Text>
        )}
      </View>
      <Text doNotScaleFonts={true}>
        {props.valueMax != null
          ? t("{{value}}/{{valueMax}}", {
              value: props.value,
              valueMax: props.valueMax,
            })
          : t("{{value}}", {
              value: props.value,
            })}
      </Text>
    </View>
  );
}

const CombatStatsRowMemo = React.memo(CombatStatsRow);

export type CombatStatsDisplay = {
  currentHP: number;
  maxHP: number;
  maxHPExplanation?: string;
  attack: number;
  attackExplanation?: string;
  attackDelay: number;
  attackDelayExplanation?: string;
  defense: number;
  defenseExplanation?: string;
  accuracy: number;
  accuracyExplanation?: string;
  dodge: number;
  dodgeExplanation?: string;
  crit: number;
  critExplanation?: string;
};

export function PlayerCombatStatsViewer(props: {
  hideText?: boolean;
  isVisible?: boolean;
}) {
  const state = useGameState();
  const maxHPUnit = useMemo(
    () => ({
      unit: t(" Max:hp:"),
    }),
    [],
  );
  const attackUnit = useMemo(
    () => ({
      unit: ":attack:",
    }),
    [],
  );
  const attackDelayUnit = useMemo(() => ({ unit: t("s") }), []);
  const defenseUnit = useMemo(
    () => ({
      unit: ":defense:",
    }),
    [],
  );
  const accuracyUnit = useMemo(
    () => ({
      unit: ":accuracy:",
    }),
    [],
  );
  const dodgeUnit = useMemo(
    () => ({
      unit: ":dodge:",
    }),
    [],
  );
  const critChanceUnit = useMemo(
    () => ({
      unit: ":critChance:",
    }),
    [],
  );
  const combatStatsDisplayObject = {
    currentHP: getCurrentPlayerHP(state),
    maxHP: calculatePlayerMaxHP(state),
    maxHPExplanation: explainPlayerCombatStat(
      state,
      CombatStat.MaxHP,
      maxHPUnit,
    ),
    attack: calculatePlayerAttack(state),
    attackExplanation: explainPlayerCombatStat(
      state,
      CombatStat.Attack,
      attackUnit,
    ),
    attackDelay: calculatePlayerAttackDelaySec(state),
    attackDelayExplanation: explainPlayerCombatStat(
      state,
      CombatStat.AttackDelay,
      attackDelayUnit,
    ),
    defense: calculatePlayerDefense(state),
    defenseExplanation: explainPlayerCombatStat(
      state,
      CombatStat.Defense,
      defenseUnit,
    ),
    accuracy: calculatePlayerAccuracyPoints(state),
    accuracyExplanation: explainPlayerCombatStat(
      state,
      CombatStat.Accuracy,
      accuracyUnit,
    ),
    dodge: calculatePlayerDodgePoints(state),
    dodgeExplanation: explainPlayerCombatStat(
      state,
      CombatStat.Dodge,
      dodgeUnit,
    ),
    crit: calculatePlayerCritPoints(state),
    critExplanation: explainPlayerCombatStat(
      state,
      CombatStat.Crit,
      critChanceUnit,
    ),
  };
  return useMemo(
    () => (
      <CombatStatsViewer
        {...props}
        showCurrentHP={true}
        combatStats={combatStatsDisplayObject}
      />
    ),
    [props.hideText, props.isVisible, JSON.stringify(combatStatsDisplayObject)],
  );
}

export function EnemyCombatStatsViewer(props: {
  enemy: Enemy;
  obfuscateValues?: boolean;
  hideText?: boolean;
  isVisible?: boolean;
}) {
  const enemy = props.enemy;
  const combatStatsDisplayObject = {
    currentHP: 0,
    maxHP: enemy.getBaseStats().maxHP,
    attack: enemy.getBaseStats().attack,
    attackDelay: enemy.getBaseStats().attackDelay,
    defense: enemy.getBaseStats().defense,
    hpRegen: 0,
    accuracy: enemy.getBaseStats().accuracy,
    dodge: enemy.getBaseStats().dodge,
    crit: enemy.getBaseStats().crit,
  };
  return useMemo(
    () => (
      <CombatStatsViewer
        {...props}
        showCurrentHP={false}
        combatStats={combatStatsDisplayObject}
      />
    ),
    [
      props.hideText,
      props.isVisible,
      props.obfuscateValues,
      JSON.stringify(combatStatsDisplayObject),
    ],
  );
}

export function CombatStatsViewer(props: {
  obfuscateValues?: boolean;
  hideText?: boolean;
  isVisible?: boolean;
  showCurrentHP?: boolean;
  combatStats: CombatStatsDisplay;
}) {
  const combatStats = props.combatStats;
  return useMemo(
    () => (
      <View style={styles.container}>
        <CombatStatsRowMemo
          stat="HP"
          icon="hp"
          hideText={props.hideText}
          value={
            props.obfuscateValues
              ? t("???")
              : props.showCurrentHP
              ? formatNumber(Math.ceil(combatStats.currentHP))
              : formatNumber(Math.ceil(combatStats.maxHP))
          }
          valueMax={
            props.showCurrentHP
              ? formatNumber(Math.ceil(combatStats.maxHP))
              : props.obfuscateValues
              ? t("???")
              : undefined
          }
          explanation={
            !props.obfuscateValues ? combatStats.maxHPExplanation : null
          }
        />
        <CombatStatsRowMemo
          stat="Attack"
          icon="attack"
          hideText={props.hideText}
          value={
            props.obfuscateValues ? t("???") : formatNumber(combatStats.attack)
          }
          explanation={
            !props.obfuscateValues ? combatStats.attackExplanation : null
          }
        />
        <CombatStatsRowMemo
          stat="Attack Delay"
          icon="attackDelay"
          hideText={props.hideText}
          value={
            props.obfuscateValues
              ? t("???")
              : t("{{value}}s", {
                  value: formatNumber(combatStats.attackDelay, {
                    showDecimals: true,
                  }),
                })
          }
          explanation={
            !props.obfuscateValues ? combatStats.attackDelayExplanation : null
          }
        />
        <CombatStatsRowMemo
          stat="Defense"
          icon="defense"
          hideText={props.hideText}
          value={
            props.obfuscateValues ? t("???") : formatNumber(combatStats.defense)
          }
          explanation={
            !props.obfuscateValues ? combatStats.defenseExplanation : null
          }
        />
        {combatStats.accuracy != 100 && (
          <CombatStatsRowMemo
            stat="Accuracy"
            icon="accuracy"
            hideText={props.hideText}
            value={
              props.obfuscateValues
                ? t("???")
                : formatNumber(combatStats.accuracy, {
                    showDecimals: false,
                  })
            }
            explanation={
              !props.obfuscateValues ? combatStats.accuracyExplanation : null
            }
          />
        )}
        {combatStats.dodge != 100 && (
          <CombatStatsRowMemo
            stat="Dodge"
            icon="dodge"
            hideText={props.hideText}
            value={
              props.obfuscateValues
                ? t("???")
                : formatNumber(combatStats.dodge, {
                    showDecimals: false,
                  })
            }
            explanation={
              !props.obfuscateValues ? combatStats.dodgeExplanation : null
            }
          />
        )}
        {combatStats.crit > 0 && (
          <CombatStatsRowMemo
            stat="Critical"
            icon="critChance"
            hideText={props.hideText}
            value={
              props.obfuscateValues
                ? t("???")
                : formatNumber(combatStats.crit, {
                    showDecimals: false,
                  })
            }
            explanation={
              !props.obfuscateValues ? combatStats.critExplanation : null
            }
          />
        )}
      </View>
    ),
    [
      props.isVisible !== false ? JSON.stringify(combatStats) : false,
      props.hideText,
      props.showCurrentHP,
    ],
  );
}

const styles = StyleSheet.create({
  container: {
    justifyContent: "center",
    alignItems: "stretch",
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-end",
  },
  inlineRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginRight: 2,
  },
});
