import { t } from "../../utils/I18n";

export enum AttackTarget {
  Player = "Player",
  Enemy = "Enemy",
  Familiar = "Familiar",
}

export function translateAttackTarget(target: AttackTarget): string {
  switch (target) {
    case AttackTarget.Player:
      return t("Player");
    case AttackTarget.Enemy:
      return t("Enemy");
    case AttackTarget.Familiar:
      return t("Familiar");
  }
}
