import { t } from "../../utils/I18n";
import { GameState, ResearcherAutoAssignStrategy } from "../GameState";
import {
  SpellElement,
  getElementLevel,
  getMaxElementLevel,
  getPrimaryElements,
  getUnlockedElements,
} from "../spells/Elements";

export function translateResearcherAutoAssignStrategy(
  strategy: ResearcherAutoAssignStrategy,
): string {
  switch (strategy) {
    case ResearcherAutoAssignStrategy.OFF:
      return t("Off");
    case ResearcherAutoAssignStrategy.SPREAD_EVENLY:
      return t("Spread evenly");
    case ResearcherAutoAssignStrategy.HIGHEST_AMOUNT:
      return t("Most researchers");
    case ResearcherAutoAssignStrategy.SPREAD_THEN_HIGHEST:
      return t("Spread, then most researchers");
  }
}

export function decideOnAutoAssigning(
  state: GameState,
): SpellElement | undefined {
  const elements = getUnlockedElements(state);
  const strategy = state.options.researcherAutoAssignStrategy;

  // First, check if any elements have no researchers
  if (strategy == ResearcherAutoAssignStrategy.SPREAD_THEN_HIGHEST) {
    for (let element of elements) {
      const assignedResearchers = state.run.studyAllocation[element];
      if (
        assignedResearchers == 0 &&
        getElementLevel(state, element) < getMaxElementLevel(state)
      ) {
        return element;
      }
    }
  }

  // Then, assign them to that which has the most
  if (
    strategy == ResearcherAutoAssignStrategy.SPREAD_THEN_HIGHEST ||
    strategy == ResearcherAutoAssignStrategy.HIGHEST_AMOUNT
  ) {
    let maxElement = null;
    let maxElementResearchers = 0;
    for (let element of elements) {
      const assignedResearchers = state.run.studyAllocation[element];
      if (
        assignedResearchers > maxElementResearchers &&
        getElementLevel(state, element) < getMaxElementLevel(state)
      ) {
        maxElement = element;
        maxElementResearchers = assignedResearchers;
      } else if (
        assignedResearchers == maxElementResearchers &&
        getPrimaryElements(state).includes(element) &&
        getElementLevel(state, element) < getMaxElementLevel(state)
      ) {
        maxElement = element;
      }
    }

    return maxElement;
  }

  // Or maybe to that which has the least
  if (strategy == ResearcherAutoAssignStrategy.SPREAD_EVENLY) {
    let minElement = null;
    let minElementResearchers = 1e99;
    for (let element of elements) {
      const assignedResearchers = state.run.studyAllocation[element];
      if (
        assignedResearchers < minElementResearchers &&
        getElementLevel(state, element) < getMaxElementLevel(state)
      ) {
        minElement = element;
        minElementResearchers = assignedResearchers;
      } else if (
        assignedResearchers == minElementResearchers &&
        getPrimaryElements(state).includes(element) &&
        getElementLevel(state, element) < getMaxElementLevel(state)
      ) {
        minElement = element;
      }
    }

    return minElement;
  }
}
