import { View } from "react-native";
import {
  getCorruption,
  getCorruptionCap,
  getCorruptionExpPenaltyMultiplier,
  getCorruptionHPPenaltyMultiplier,
  getCorruptionTransmutePenaltyMultiplier,
  isCorruptionExpPenaltyActive,
  isCorruptionHPPenaltyActive,
  isCorruptionTransmutePenaltyActive,
} from "../../backend/rituals/Rituals";
import { formatMultiplier, formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Markdown } from "../utility/Markdown";
import Styles from "../utility/Styles";
import { useGameState } from "../utility/useGameState";

export function CorruptionDetails(props: {}) {
  const state = useGameState();
  const isHPPenaltyActive = isCorruptionHPPenaltyActive(state);
  const isExpPenaltyActive = isCorruptionExpPenaltyActive(state);
  const isTransmutePenaltyActive = isCorruptionTransmutePenaltyActive(state);

  const corruptionAmount = getCorruption(state);
  const corruptionCap = getCorruptionCap(state);

  const penalties = [];

  if (isHPPenaltyActive) {
    penalties.push(
      t(`* {{multiplier}} Max:hp:`, {
        multiplier: formatMultiplier(getCorruptionHPPenaltyMultiplier(state)),
      }),
    );
  }
  if (isExpPenaltyActive) {
    penalties.push(
      t(`* {{multiplier}} Element Exp gained`, {
        multiplier: formatMultiplier(getCorruptionExpPenaltyMultiplier(state)),
      }),
    );
  }
  if (isTransmutePenaltyActive) {
    penalties.push(
      t(
        `* +{{multiplier}}% resources and items needed for all Transmutation (rounded up)`,
        {
          multiplier: formatNumber(
            getCorruptionTransmutePenaltyMultiplier(state) * 100 - 100,
            { showDecimals: true },
          ),
        },
      ),
    );
  }

  const penaltyText = penalties.length > 0 ? penalties.join("\n") : "";

  const corruptionExplanation = t(
    `Your active Rituals will give you Corruption (:corruption:). Your soul cannot take on too much :corruption: at a time; if you activate too many Rituals at once, you will grow weak.`,
  );
  const corruptionPenalties =
    corruptionAmount <= corruptionCap
      ? t(
          `You currently have {{amount}}:corruption:, which is *below* your safe limit of {{cap}}:corruption:. Your soul is strong; you have no penalties.`,
          {
            amount: formatNumber(corruptionAmount, { showDecimals: true }),
            cap: formatNumber(corruptionCap, { showDecimals: true }),
          },
        )
      : t(
          `You currently have {{amount}}:corruption:, which is *above* your safe limit of {{cap}}:corruption:. The extra Corruption is giving you the following penalties:
  
{{penaltyText}}`,
          {
            amount: formatNumber(corruptionAmount, { showDecimals: true }),
            cap: formatNumber(corruptionCap, { showDecimals: true }),
            penaltyText,
          },
        );
  return (
    <View>
      <View>
        <Markdown>{corruptionExplanation}</Markdown>
      </View>
      <View style={Styles.mt8}>
        <Markdown>{corruptionPenalties}</Markdown>
      </View>
    </View>
  );
}
