import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import {
  getResourceAmount,
  getResourceCap,
  grantResource,
  Resource,
} from "../../Resources";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Action, ActionEffect, DoActionArgs } from "../Action";
import { ActionArea, ActionSubcategories } from "../ActionAreas";

class GatherMana extends Action {
  constructor() {
    super();
  }

  getId(): string {
    return "gatherMana";
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategories[]>> {
    return { HOME: [ActionSubcategories.ACTIONS] };
  }

  getDisplayName(state: GameState): string {
    return t("Gather Mana");
  }

  isVisible(): boolean {
    return true;
  }

  isEnabled(state: GameState): boolean {
    return (
      getResourceAmount(state, Resource.Mana) <
      getResourceCap(state, Resource.Mana)
    );
  }

  getTags(): string[] {
    return [
      ...super.getTags(),
      TransformationTags.ManualGathering,
      TransformationTags.Production,
      Resource.Mana,
    ];
  }

  getDisplayDescription(): string {
    return t(
      "Focus your thoughts and gather mana. *Tip: You can press and hold buttons to auto-press them!*",
    );
  }

  getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      mana: { value: 1 },
    };
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    return t(`+{{amount}}:mana:`, {
      amount: formatNumber(effects.mana, {
        showDecimals: true,
      }),
    });
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return { resources: {}, items: {} };
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    return grantResource(
      Resource.Mana,
      this.getActionEffects(state).mana,
    )(state);
  }
}

export default new GatherMana();
