import memoize from "sonic-memoize";
import { addRecords, clone, fromEntries, keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { cacheIndefinitely, clearCacheKey } from "../Caching";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasFlag, setFlag } from "../Flags";
import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  grantResource,
} from "../Resources";
import { registerPostRetirementListener } from "../Retire";
import { SpellElement } from "../spells/Elements";
import { Spell } from "../spells/Spell";
import { Spells, registerSpellCacheClearedListener } from "../spells/Spells";
import { registerTimeTickListener } from "../timetick/TimeTick";
import {
  applyTransformations,
  explainTransformationsText,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { AutocastCondition, AutocastConditions } from "./AutocastCondition";
import { loadAutocastConditions } from "./conditions/AutocastConditionLoader";
import SmartlyAutocastCondition from "./conditions/SmartlyAutocastCondition";

export enum SpellAutocastCategory {
  TRANSMUTATION = "TRANSMUTATION",
  CHANNELING = "CHANNELING",
  GENERAL_BUFF = "GENERAL_BUFF",
  COMBAT_BUFF = "COMBAT_BUFF",
  ATTACK = "ATTACK",
  HEALING = "HEALING",
  OTHER = "OTHER",
}

export type AutocastRule = {
  id: string;
  spellId: string;
  disabled: boolean;
  wizardsAssigned: number;
  conditions: AutocastConditionData[];
};

export type AutocastRuleStatus = {
  wizardsAssigned: number;
  disabled: boolean;
  currentPoints: number;
  hidden: boolean;
};

export type AutocastStrategy = {
  rules: AutocastRule[];
  permaCastRules: Record<string, AutocastRule>;
  wizardsAssignedToPermaCast: number;
  name: string;
};

export type AutocastConditionData = {
  condition: string;
  values: string[];
};

export function translateSpellAutocastCategory(
  category: SpellAutocastCategory,
): string {
  switch (category) {
    case SpellAutocastCategory.ATTACK:
      return t("Attack Spell");
    case SpellAutocastCategory.CHANNELING:
      return t("Channeling");
    case SpellAutocastCategory.COMBAT_BUFF:
      return t("Combat Buff / Debuff");
    case SpellAutocastCategory.GENERAL_BUFF:
      return t("General Buff / Debuff");
    case SpellAutocastCategory.HEALING:
      return t("Healing");
    case SpellAutocastCategory.TRANSMUTATION:
      return t("Transmutation");
    case SpellAutocastCategory.OTHER:
      return t("Other");
    // Do not add a default
  }
}

const productionTags = [TransformationTags.AutocastPointsProduction];
const productionUnit = {
  unit: ":wizardpower:",
};
export function getAutocastPointIncomePerSecPerWizard(state: GameState) {
  return applyTransformations(productionTags, state, 1.0);
}
export function explainAutocastPointIncomePerSecPerWizard(state: GameState) {
  return explainTransformationsText(productionTags, state, 1.0, productionUnit);
}

const wizardAmountTags = [TransformationTags.WizardAmount];
export function getWizardAmount(state: GameState) {
  return Math.floor(applyTransformations(wizardAmountTags, state, 1.0));
}
const wizardAmountUnit = memoize(() => ({
  unit: t(" wizard(s)"),
}));
export function explainWizardAmount(state: GameState) {
  return explainTransformationsText(
    wizardAmountTags,
    state,
    1.0,
    wizardAmountUnit(),
  );
}

function getUsedWizards(state: GameState) {
  let usedWizards = 0;
  const strategy = getCurrentAutocastStrategy(state);
  for (let idx = 0; idx < strategy.rules.length; idx++) {
    usedWizards += getAssignedWizardsByRuleIndex(state, idx) ?? 0;
  }
  usedWizards += strategy.wizardsAssignedToPermaCast;
  return usedWizards;
}

export function getFreeWizards(state: GameState) {
  return Math.floor(getWizardAmount(state) - getUsedWizards(state));
}

export function getAutocastPointCap(state: GameState, rule: AutocastRule) {
  return (
    Spells.getByIdNullable(rule.spellId)?.getAutocastPointsRequirement(state) ??
    0
  );
}

function grantAutocastPoints(
  state: GameState,
  ruleIdx: number,
  amount: number,
) {
  if (amount == 0) {
    return state;
  }
  const rule = getCurrentAutocastStrategy(state).rules[ruleIdx];
  return grantAutocastPointsToRule(
    state,
    rule,
    amount,
    Spells.getByIdNullable(rule.spellId),
  );
}

function grantAutocastPointsToRule(
  state: GameState,
  rule: AutocastRule,
  amount: number,
  spell: Spell,
) {
  const ruleStatus = getAutocastStatusByRule(state, rule);
  ruleStatus.currentPoints = Math.min(
    spell.getAutocastPointsRequirement(state),
    Math.max(0, ruleStatus.currentPoints + amount),
  );
  state.run.autocast.rules[rule.id] = ruleStatus;
  return state;
}

export function assignWizardsToPermaCast(state: GameState, amount: number) {
  const strategy = getCurrentAutocastStrategy(state);
  const freeWizards = getFreeWizards(state);
  const amountToAdd = Math.min(freeWizards, amount);
  strategy.wizardsAssignedToPermaCast = Math.max(
    0,
    strategy.wizardsAssignedToPermaCast + amountToAdd,
  );
  return state;
}

export function permaCastWizardsNeeded(state: GameState, spell: Spell): number {
  const wizardPowerProductionPerWizard =
    getAutocastPointIncomePerSecPerWizard(state);
  const wizardPowerNeeded = permaCastWizardPowerNeeded(state, spell);
  const wizardsNeeded = Math.ceil(
    wizardPowerNeeded / wizardPowerProductionPerWizard,
  );
  return wizardsNeeded;
}

export function permaCastWizardPowerNeeded(
  state: GameState,
  spell: Spell,
): number {
  const delay = spell.getPermaCastDelaySec(state);
  const wizardPowerConsumption = spell.getAutocastPointsRequirement(state);
  return wizardPowerConsumption / delay;
}

export function addSpellToPermaCast(state: GameState, spell: Spell): GameState {
  if (!spell.isPermaCastAllowed()) {
    return state;
  }
  const strategy = getCurrentAutocastStrategy(state);
  const rule = getAutocastRuleBySpell(state, spell);
  if (rule != null) {
    strategy.rules = strategy.rules.filter(
      (strategyRule) => rule != strategyRule,
    );
  }
  strategy.permaCastRules[spell.getId()] = {
    conditions: DEFAULT_CONDITIONS,
    disabled: false,
    id: Math.random().toString(),
    spellId: spell.getId(),
    wizardsAssigned: 0,
  };
  state = clearAutocastCaches(state);
  const totalWizardsNeeded = estimateWizardsNeededPermaCast(state, strategy);
  const currentWizards = strategy.wizardsAssignedToPermaCast;
  if (currentWizards < totalWizardsNeeded) {
    state = assignWizardsToPermaCast(
      state,
      totalWizardsNeeded - currentWizards,
    );
  }
  clearAutocastCaches(state);
  return state;
}

export function removeSpellFromPermaCast(
  state: GameState,
  spell: Spell,
): GameState {
  const strategy = getCurrentAutocastStrategy(state);
  if (!strategy.permaCastRules[spell.getId()]) {
    return state;
  }
  const totalWizardsNeededBefore = estimateWizardsNeededPermaCast(
    state,
    strategy,
  );
  const currentWizards = strategy.wizardsAssignedToPermaCast;
  delete strategy.permaCastRules[spell.getId()];
  state = clearAutocastCaches(state);
  const totalWizardsNeededAfter = estimateWizardsNeededPermaCast(
    state,
    strategy,
  );
  // Only keep it "synced" if it was already synced in the first place
  if (
    currentWizards == totalWizardsNeededBefore &&
    currentWizards > totalWizardsNeededAfter
  ) {
    state = assignWizardsToPermaCast(
      state,
      totalWizardsNeededAfter - currentWizards,
    );
  }
  return state;
}

export function isSpellInWizards(state: GameState, spell: Spell): boolean {
  const rule = getAutocastRuleBySpell(state, spell);
  if (rule == null) {
    return false;
  }
  const status = getAutocastStatusByRule(state, rule);
  return status.wizardsAssigned > 0;
}

export function isSpellInPermaCast(state: GameState, spell: Spell): boolean {
  const strategy = getCurrentAutocastStrategy(state);
  return strategy.permaCastRules[spell.getId()] != null;
}

export function assignWizardsToRuleIndex(
  state: GameState,
  ruleIdx: number,
  amount: number,
) {
  const rule = getCurrentAutocastStrategy(state).rules[ruleIdx];
  return assignWizardsToRule(state, rule, amount);
}

export function assignWizardsToRule(
  state: GameState,
  rule: AutocastRule,
  amount: number,
) {
  const ruleStatus = getAutocastStatusByRule(state, rule);
  if (ruleStatus == null || rule == null) {
    return state;
  }
  const freeWizards = getFreeWizards(state);
  const amountToAdd = Math.min(freeWizards, amount);
  ruleStatus.wizardsAssigned = Math.max(
    0,
    ruleStatus.wizardsAssigned + amountToAdd,
  );
  rule.wizardsAssigned = ruleStatus.wizardsAssigned;
  state.run.autocast.rules[rule.id] = ruleStatus;
  state = setFlag(Flag.AssignedWizard)(state);

  return state;
}

export function toggleAutocastRuleDisabledIndex(
  state: GameState,
  ruleIdx: number,
) {
  return toggleAutocastRuleDisabled(
    state,
    getCurrentAutocastStrategy(state).rules[ruleIdx],
  );
}

export function toggleAutocastRuleDisabled(
  state: GameState,
  rule: AutocastRule,
) {
  const ruleStatus = getAutocastStatusByRule(state, rule);
  ruleStatus.disabled = !ruleStatus.disabled;
  rule.disabled = ruleStatus.disabled;
  state.run.autocast.rules[rule.id] = ruleStatus;
  return state;
}

export function toggleAutocastRuleHiddenIndex(
  state: GameState,
  ruleIdx: number,
) {
  const rule = getCurrentAutocastStrategy(state).rules[ruleIdx];
  return toggleAutocastRuleHidden(state, rule);
}

export function toggleAutocastRuleHidden(state: GameState, rule: AutocastRule) {
  const ruleStatus = getAutocastStatusByRule(state, rule);
  ruleStatus.hidden = !ruleStatus.hidden;
  state.run.autocast.rules[rule.id] = ruleStatus;
  return state;
}

export function unhideAllRules(state: GameState) {
  for (let id in state.run.autocast.rules) {
    state.run.autocast.rules[id].hidden = false;
  }
  return state;
}

export function getCurrentAutocastStrategy(state: GameState): AutocastStrategy {
  return state.global.autocast.currentStrategy;
}

export function getAutocastStatusByRuleIndex(
  state: GameState,
  index: number,
): AutocastRuleStatus {
  const rule = getCurrentAutocastStrategy(state).rules[index];
  return getAutocastStatusByRule(state, rule);
}

export function getAutocastRuleIndexByRule(
  state: GameState,
  rule: AutocastRule,
): number {
  return getCurrentAutocastStrategy(state).rules.findIndex(
    (value) => value.id == rule.id,
  );
}

export function getAutocastStatusByRule(
  state: GameState,
  rule: AutocastRule,
): AutocastRuleStatus {
  return (
    state.run.autocast.rules?.[rule?.id] ?? {
      currentPoints: 0,
      disabled: false,
      wizardsAssigned: 0,
      hidden: false,
    }
  );
}

export function getAutocastRuleBySpell(
  state: GameState,
  spell: Spell,
  strategy?: AutocastStrategy,
): AutocastRule | null {
  if (strategy == null) {
    strategy = getCurrentAutocastStrategy(state);
  }
  for (let rule of strategy.rules) {
    if (rule.spellId == spell.getId()) {
      return rule;
    }
  }
  return null;
}

export function getAutocastRuleById(
  state: GameState,
  ruleId: string,
): AutocastRule | null {
  const strategy = getCurrentAutocastStrategy(state);
  for (let rule of strategy.rules) {
    if (rule.id == ruleId) {
      return rule;
    }
  }
  return null;
}

export function getAssignedWizardsByRuleIndex(
  state: GameState,
  index: number,
): number {
  return getAutocastStatusByRuleIndex(state, index).wizardsAssigned;
}

export function saveCurrentAutocastStrategy(
  state: GameState,
  name: string,
  index: number,
): GameState {
  state.global.autocast.currentStrategy.name = name;
  state.global.autocast.savedStrategies[index] = clone(
    state.global.autocast.currentStrategy,
  );
  state.global.autocast.lastStrategyIndex = index;
  return state;
}

export function clearAutocastStrategy(
  state: GameState,
  index: number,
): GameState {
  delete state.global.autocast.savedStrategies[index];
  return state;
}

export function loadAutocastStrategy(
  state: GameState,
  index: number,
): GameState {
  const strategyToLoad = state.global.autocast.savedStrategies?.[index];
  if (strategyToLoad == null) {
    return state;
  }

  const oldStrategy = state.global.autocast.currentStrategy;
  const oldRuleStatuses = state.run.autocast.rules;
  state.global.autocast.currentStrategy = clone(strategyToLoad);
  state.run.autocast.rules = {};

  const permaCastWizards = strategyToLoad.wizardsAssignedToPermaCast;

  // Go bit by bit assigning wizards and setting status
  for (let idx in state.global.autocast.currentStrategy.rules) {
    const rule = state.global.autocast.currentStrategy.rules[idx];

    const spell = Spells.getById(rule.spellId);
    const oldRule = getAutocastRuleBySpell(state, spell, oldStrategy);

    let progress = 0;
    if (oldRule != null) {
      progress = oldRuleStatuses[oldRule.id]?.currentPoints ?? 0;
    }

    const ruleStatus = getAutocastStatusByRule(state, rule);
    ruleStatus.disabled = rule.disabled;
    ruleStatus.currentPoints = progress;
    state.run.autocast.rules[rule.id] = ruleStatus;
    state = assignWizardsToRule(state, rule, rule.wizardsAssigned);
  }

  state.global.autocast.currentStrategy.wizardsAssignedToPermaCast = 0;
  state = assignWizardsToPermaCast(state, permaCastWizards);

  // Transfer over the progress of all permacast rules
  for (let spellId of keysOf(oldStrategy.permaCastRules)) {
    const oldRule = oldStrategy.permaCastRules[spellId];
    const oldRuleId = oldRule.id;
    const newRule =
      state.global.autocast.currentStrategy.permaCastRules[spellId];
    if (newRule != null) {
      const oldRuleProgress = oldRuleStatuses[oldRuleId]?.currentPoints ?? 0;
      const newStatus = getAutocastStatusByRule(state, newRule);
      newStatus.currentPoints = oldRuleProgress;
      state.run.autocast.rules[newRule.id] = newStatus;
    }
  }

  state.global.autocast.lastStrategyIndex = index;
  state = clearAutocastCaches(state);

  return state;
}

export function getConditionFromData(
  conditionData: AutocastConditionData,
): AutocastCondition {
  return AutocastConditions.getById(conditionData.condition);
}

function conditionIsMet(
  state: GameState,
  rule: AutocastRule,
  conditionData: AutocastConditionData,
): boolean {
  const condition = getConditionFromData(conditionData);
  const spell = Spells.getByIdNullable(rule.spellId);

  return condition.isConditionMet(state, spell, conditionData.values);
}

export function isAutocastUnlocked(state: GameState) {
  return hasFlag(state, Flag.WizardsUnlocked);
}

export function isAutocastEverUnlocked(state: GameState) {
  return hasEverHadFlag(state, Flag.WizardsUnlocked);
}

export function estimateIntervalBetweenCastsForRule(
  state: GameState,
  rule: AutocastRule,
  ruleStatus: AutocastRuleStatus,
): number {
  const spell = Spells.getByIdNullable(rule?.spellId);
  if (spell == null) {
    return 0;
  }

  if (ruleStatus.wizardsAssigned <= 0) {
    return 0;
  }

  const minimumFrequency = spell.getEstimatedMinAutocastDelay(state, rule);
  const timeToFull =
    spell.getAutocastPointsRequirement(state) /
    (ruleStatus.wizardsAssigned * getAutocastPointIncomePerSecPerWizard(state));

  return Math.max(minimumFrequency, timeToFull);
}

export function estimateIntervalBetweenCastsForRuleIndex(
  state: GameState,
  rule: AutocastRule,
  index: number,
): number {
  return estimateIntervalBetweenCastsForRule(
    state,
    rule,
    getAutocastStatusByRuleIndex(state, index),
  );
}

export function estimateManaConsumptionPerSecPerRule(
  state: GameState,
  rule: AutocastRule,
  ruleStatus: AutocastRuleStatus,
): number {
  const spell = Spells.getByIdNullable(rule?.spellId);
  if (spell == null) {
    return 0;
  }

  if (ruleStatus.wizardsAssigned <= 0 || rule.disabled) {
    return 0;
  }

  const manaCost = spell.getCost(state).resources[Resource.Mana] ?? 0;
  return (
    manaCost / estimateIntervalBetweenCastsForRule(state, rule, ruleStatus)
  );
}

export function estimateManaConsumptionPerSecPerRuleIndex(
  state: GameState,
  rule: AutocastRule,
  index: number,
): number {
  return estimateManaConsumptionPerSecPerRule(
    state,
    rule,
    getAutocastStatusByRuleIndex(state, index),
  );
}

export function estimateExpGainPerSecPerRule(
  state: GameState,
  rule: AutocastRule,
  index: number,
): Partial<Record<SpellElement, number>> {
  const spell = Spells.getByIdNullable(rule.spellId);
  if (spell == null) {
    return {};
  }

  if (getAssignedWizardsByRuleIndex(state, index) <= 0 || rule.disabled) {
    return {};
  }

  const expGain = spell.getExpFromUsage(state);
  const intervalBetweenCasts = estimateIntervalBetweenCastsForRuleIndex(
    state,
    rule,
    index,
  );
  return fromEntries(
    keysOf(expGain).map((element) => [
      element,
      expGain[element] / intervalBetweenCasts,
    ]),
  );
}

export function estimateManaConsumptionPerSecPermaCast(
  state: GameState,
  strategy: AutocastStrategy,
): number {
  if (strategy == getCurrentAutocastStrategy(state)) {
    return cacheIndefinitely(
      state,
      (_s) => "currentAutocastPermaCastMana",
      (state) =>
        estimateManaConsumptionPerSecPermaCastUncached(state, strategy),
    );
  }
  return estimateManaConsumptionPerSecPermaCastUncached(state, strategy);
}

function estimateManaConsumptionPerSecPermaCastUncached(
  state: GameState,
  strategy: AutocastStrategy,
): number {
  return keysOf(strategy.permaCastRules)
    .filter((spellId) => {
      const spell = Spells.getById(spellId);
      return (
        spell.isVisible(state) &&
        spell.getManaCost(state) <= getResourceCap(state, Resource.Mana)
      );
    })
    .map((spellId) => Spells.getById(spellId).getPermaCastManaCostPerSec(state))
    .reduce((prev, current) => prev + current, 0);
}

export function estimateWizardPowerConsumptionPerSecPermaCast(
  state: GameState,
  strategy: AutocastStrategy,
): number {
  const uncached = (state) =>
    keysOf(strategy.permaCastRules)
      .filter((spellId) => {
        const spell = Spells.getById(spellId);
        return (
          spell.isVisible(state) &&
          spell.getManaCost(state) <= getResourceCap(state, Resource.Mana)
        );
      })
      .map((spellId) =>
        Spells.getById(spellId).getPermaCastAutocastPointsConsumptionPerSec(
          state,
        ),
      )
      .reduce((prev, current) => prev + current, 0);
  if (getCurrentAutocastStrategy(state) != strategy) {
    return uncached(strategy);
  }
  return cacheIndefinitely(
    state,
    (_s) => "permacastWizardPowerEstimate",
    uncached,
  );
}

export function clearAutocastCaches(state: GameState): GameState {
  state = clearCacheKey(state, (_state) => "permacastWizardPowerEstimate");
  state = clearCacheKey(state, (_state) => "currentAutocastPermaCastMana");
  state = clearPermacastCache(state);
  return state;
}

export function estimateWizardsNeededPermaCast(
  state: GameState,
  strategy: AutocastStrategy,
): number {
  return Math.ceil(
    estimateWizardPowerConsumptionPerSecPermaCast(state, strategy) /
      getAutocastPointIncomePerSecPerWizard(state),
  );
}

export function estimateManaConsumptionPerSecPerStrategy(
  state: GameState,
  strategy: AutocastStrategy,
): number {
  let cumulativeManaConsumption = 0;

  for (let idx in strategy.rules) {
    cumulativeManaConsumption += estimateManaConsumptionPerSecPerRuleIndex(
      state,
      strategy.rules[idx],
      Number.parseInt(idx),
    );
  }

  cumulativeManaConsumption += estimateManaConsumptionPerSecPermaCast(
    state,
    strategy,
  );

  return cumulativeManaConsumption;
}

export function estimateExpGainPerSecPerStrategy(
  state: GameState,
  strategy: AutocastStrategy,
): Partial<Record<SpellElement, number>> {
  return strategy.rules.reduce(
    (cumulative, currentRule, idx) =>
      addRecords([
        cumulative,
        estimateExpGainPerSecPerRule(state, currentRule, idx),
      ]),
    {},
  );
}

export function getSortedPermaCastSpellList(
  state: GameState,
  strategy: AutocastStrategy,
): Spell[] {
  const permaCastSpells = keysOf(strategy.permaCastRules)
    .map((spellId) => Spells.getById(spellId))
    .filter((spell) => spell.isVisible(state));
  const permaCastSpellRequiredWizardPower = {};
  const permaCastSpellRequiredMana = {};
  for (let spell of permaCastSpells) {
    permaCastSpellRequiredWizardPower[spell.getId()] =
      spell.getPermaCastAutocastPointsConsumptionPerSec(state);
    permaCastSpellRequiredMana[spell.getId()] =
      spell.getPermaCastManaCostPerSec(state);
  }
  // Sort by required wizard power * required Mana
  const sortedPermaCastSpells = permaCastSpells.sort(
    (a, b) =>
      permaCastSpellRequiredWizardPower[a.getId()] *
        permaCastSpellRequiredMana[a.getId()] -
      permaCastSpellRequiredWizardPower[b.getId()] *
        permaCastSpellRequiredMana[b.getId()],
  );
  return sortedPermaCastSpells;
}

const DEFAULT_CONDITIONS = [
  {
    condition: SmartlyAutocastCondition.getId(),
    values: [],
  },
];

function clearPermacastCache(state: GameState): GameState {
  return clearCacheKey(state, (_state) => "permacast");
}

function calculatePermacastInfo(state: GameState, strategy: AutocastStrategy) {
  const permaCastSpells = keysOf(strategy.permaCastRules).map((spellId) =>
    Spells.getById(spellId),
  );
  return cacheIndefinitely(
    state,
    (_state) => "permacast",
    (state) => {
      const permaCastSpellRequiredWizardPower = {};
      const permaCastSpellRequiredMana = {};
      for (let spell of permaCastSpells) {
        permaCastSpellRequiredWizardPower[spell.getId()] =
          spell.getPermaCastAutocastPointsConsumptionPerSec(state);
        permaCastSpellRequiredMana[spell.getId()] =
          spell.getPermaCastManaCostPerSec(state);
      }
      // Sort by required wizard power * required Mana
      const sortedPermaCastSpells = permaCastSpells.sort(
        (a, b) =>
          permaCastSpellRequiredWizardPower[a.getId()] *
            permaCastSpellRequiredMana[a.getId()] -
          permaCastSpellRequiredWizardPower[b.getId()] *
            permaCastSpellRequiredMana[b.getId()],
      );
      return {
        permaCastSpellRequiredWizardPower,
        permaCastSpellRequiredMana,
        sortedPermaCastSpells,
      };
    },
  );
}

export function loadAutocast() {
  loadAutocastConditions();
  registerTimeTickListener(
    "Autocast",
    (state, timeElapsedSec, isWarp) => {
      if (!isAutocastUnlocked(state)) {
        return state;
      }

      if (state.run.autocast.autocastPaused) {
        return state;
      }

      state.temp.isInAutocast = true;

      // Grant autocast points
      const strategy = getCurrentAutocastStrategy(state);
      const income = getAutocastPointIncomePerSecPerWizard(state);
      for (let idx = 0; idx < strategy.rules.length; idx++) {
        state = grantAutocastPoints(
          state,
          idx,
          income *
            (getAssignedWizardsByRuleIndex(state, idx) ?? 0) *
            timeElapsedSec,
        );
      }

      // Do the actual autocasting
      for (let index = 0; index < strategy.rules.length; index++) {
        const rule = strategy.rules[index];
        const ruleStatus = getAutocastStatusByRuleIndex(state, index);
        if (ruleStatus.disabled || ruleStatus.wizardsAssigned == 0) {
          continue;
        }
        if (rule.spellId == "checkOnly") {
          if (
            !rule.conditions.every((condition) =>
              conditionIsMet(state, rule, condition),
            )
          ) {
            break;
          }
          continue;
        }

        const spell = Spells.getById(rule.spellId);
        const pointsRequired = spell.getAutocastPointsRequirement(state);
        if (ruleStatus.currentPoints < pointsRequired) {
          continue;
        }
        if (!spell.isVisible(state)) {
          continue;
        }
        if (!spell.isEnabled(state)) {
          continue;
        }

        const conditions =
          rule.conditions.length > 0 ? rule.conditions : DEFAULT_CONDITIONS;
        if (
          conditions.every((condition) =>
            conditionIsMet(state, rule, condition),
          )
        ) {
          state = spell.doAction({ isAutomatic: true }, state);
          state = grantAutocastPoints(state, index, -pointsRequired);
        }
      }

      // Handle permacast
      // First, calculate total required wizard power
      const {
        permaCastSpellRequiredWizardPower,
        permaCastSpellRequiredMana,
        sortedPermaCastSpells,
      } = calculatePermacastInfo(state, strategy);

      let availableWizardPower =
        strategy.wizardsAssignedToPermaCast * timeElapsedSec * income;
      const maxMana = getResourceCap(state, Resource.Mana);

      state.temp.isInPermacast = true;
      for (let spell of sortedPermaCastSpells) {
        const spellId = spell.getId();
        const rule = strategy.permaCastRules[spellId];
        const ruleStatus = getAutocastStatusByRule(state, rule);

        if (ruleStatus.disabled) {
          continue;
        }

        const currentMana = getResourceAmount(state, Resource.Mana);
        const manaCostToPay =
          permaCastSpellRequiredMana[spellId] * timeElapsedSec;

        // There was historically a subtle bug here.
        // The bug happens because incomes were calculated after Autocast.
        // It's a problem because if you have a heavy load and little remaining Mana,
        // Autocast can become delayed due to there not being enough Mana left until
        // the income is granted.
        // We cannot make income be calculated before Autocast, because that will result
        // on a Mana bar that never fully fills up.
        // The solution is that Autocast should be split into two. We should process everything
        // before incomes, until we find a case where we have not enough Mana. Then we should
        // break and continue processing the rest of Permacast *after* the incomes. If we find this
        // situation, the Mana bar will probably not be full anyway so it's not a problem.
        if (currentMana < manaCostToPay) {
          state.temp.lastProcessedAutocastSpellId = spellId;
          break;
        }

        if (spell.getManaCost(state) > maxMana) {
          // Skip
          continue;
        }
        if (!spell.isVisible(state)) {
          // Also skip
          continue;
        }

        const wizardPowerToAssign = Math.min(
          availableWizardPower,
          permaCastSpellRequiredWizardPower[spellId] * timeElapsedSec,
        );

        // If we do not have enough wizards, do not drain Mana
        if (availableWizardPower > 0) {
          state = grantAutocastPointsToRule(
            state,
            rule,
            wizardPowerToAssign,
            spell,
          );
          state = grantResource(Resource.Mana, -manaCostToPay)(state);
        }
        availableWizardPower -= wizardPowerToAssign;

        const pointsRequired = spell.getAutocastPointsRequirement(state);
        if (ruleStatus.currentPoints < pointsRequired) {
          continue;
        }
        if (!spell.isEnabled(state, true)) {
          continue;
        }

        const conditions =
          rule.conditions.length > 0 ? rule.conditions : DEFAULT_CONDITIONS;
        if (
          conditions.every((condition) =>
            conditionIsMet(state, rule, condition),
          )
        ) {
          state = spell.doActionPermaCast(state);
          state = grantAutocastPointsToRule(
            state,
            rule,
            -pointsRequired,
            spell,
          );
        }
      }
      state.temp.isInPermacast = false;

      state.temp.isInAutocast = false;

      return state;
    },
    -10,
  );

  registerTimeTickListener(
    "autocastPart2",
    (state, timeElapsedSec, isWarp) => {
      if (!isAutocastUnlocked(state)) {
        return state;
      }

      if (state.temp.lastProcessedAutocastSpellId == null) {
        return state;
      }

      state.temp.isInAutocast = true;

      const strategy = getCurrentAutocastStrategy(state);
      const income = getAutocastPointIncomePerSecPerWizard(state);

      // Handle permacast
      // First, calculate total required wizard power
      const {
        permaCastSpellRequiredWizardPower,
        permaCastSpellRequiredMana,
        sortedPermaCastSpells,
      } = calculatePermacastInfo(state, strategy);

      let availableWizardPower =
        strategy.wizardsAssignedToPermaCast * timeElapsedSec * income;
      const maxMana = getResourceCap(state, Resource.Mana);

      let foundTheSpell = false;
      state.temp.isInPermacast = true;
      for (let spell of sortedPermaCastSpells) {
        // Continue until we find the spell
        const spellId = spell.getId();
        const wizardPowerToAssign = Math.min(
          availableWizardPower,
          permaCastSpellRequiredWizardPower[spellId] * timeElapsedSec,
        );
        if (
          !foundTheSpell &&
          spellId != state.temp.lastProcessedAutocastSpellId
        ) {
          availableWizardPower -= wizardPowerToAssign;
          continue;
        } else {
          foundTheSpell = true;
        }

        const rule = strategy.permaCastRules[spellId];
        const ruleStatus = getAutocastStatusByRule(state, rule);

        if (ruleStatus.disabled) {
          continue;
        }

        const currentMana = getResourceAmount(state, Resource.Mana);
        const manaCostToPay =
          permaCastSpellRequiredMana[spellId] * timeElapsedSec;
        if (currentMana < manaCostToPay) {
          // Now we are safe to skip here
          continue;
        }

        if (spell.getManaCost(state) > maxMana) {
          // Skip
          continue;
        }
        if (!spell.isVisible(state)) {
          // Also skip
          continue;
        }

        // If we do not have enough wizards, do not drain Mana
        if (availableWizardPower > 0) {
          state = grantAutocastPointsToRule(
            state,
            rule,
            wizardPowerToAssign,
            spell,
          );
          state = grantResource(Resource.Mana, -manaCostToPay)(state);
        }
        availableWizardPower -= wizardPowerToAssign;

        const pointsRequired = spell.getAutocastPointsRequirement(state);
        if (ruleStatus.currentPoints < pointsRequired) {
          continue;
        }
        if (!spell.isEnabled(state, true)) {
          continue;
        }

        const conditions =
          rule.conditions.length > 0 ? rule.conditions : DEFAULT_CONDITIONS;
        if (
          conditions.every((condition) =>
            conditionIsMet(state, rule, condition),
          )
        ) {
          state = spell.doActionPermaCast(state);
          state = grantAutocastPointsToRule(
            state,
            rule,
            -pointsRequired,
            spell,
          );
        }
      }

      state.temp.isInPermacast = false;
      state.temp.isInAutocast = false;
      state.temp.lastProcessedAutocastSpellId = undefined;

      return state;
    },
    20,
  );

  registerPostRetirementListener("permaCastRetirement", (state, isMock) => {
    // After retirement, we will have no wizards for any Perma-Cast.
    // So unassign all of them, and clear all spells from Perma-Cast.
    // Avid players will make a strategy anyway and load it.
    const strategy = getCurrentAutocastStrategy(state);
    strategy.permaCastRules = {};
    strategy.wizardsAssignedToPermaCast = 0;
    return state;
  });

  addGameStateListener("wizardRemover", (state) => {
    const freeWizards = getFreeWizards(state);
    if (freeWizards < 0) {
      return (state) => {
        let freeWizards = getFreeWizards(state);
        const rules = getCurrentAutocastStrategy(state).rules;
        const ruleLength = rules.length;
        let currentRuleIdx = ruleLength - 1;
        while (freeWizards < 0 && currentRuleIdx >= 0) {
          const assignedWizards = getAssignedWizardsByRuleIndex(
            state,
            currentRuleIdx,
          );
          if (assignedWizards > 0) {
            state = assignWizardsToRuleIndex(state, currentRuleIdx, -1);
            freeWizards++;
          } else {
            currentRuleIdx--;
          }
        }
        if (freeWizards < 0) {
          state = assignWizardsToPermaCast(state, freeWizards);
        }
        return state;
      };
    }
  });

  registerSpellCacheClearedListener(clearAutocastCaches);
}
