import memoize from "sonic-memoize";
import { AttackTarget } from "../exploration/AttackTarget";
import { clearCombatStatCache, CombatStat } from "../exploration/CombatStats";
import { hasWorldFlag, setWorldFlag } from "../Flags";
import { GameState, GameStateTransform } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  applyTransformations,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";

const TemporaryEffects = new DataStore<TemporaryEffect>();
export { TemporaryEffects };

const TEMPORARY_EFFECT_DELTA = 1e10;
const CLEAR_EPSILON = TEMPORARY_EFFECT_DELTA / 100;

export type TemporaryEffectData = {
  id: string;
  setTime: number;
  endTime: number;
  target: AttackTarget;
  params: Record<string, any>;
  fromAutocast: boolean;
  fromPermacast: boolean;
  fromPouch: boolean;
  registeredClear: boolean;
};

function getLookupId(id: string, target?: AttackTarget): string {
  return id + (target ?? AttackTarget.Player);
}

export function getTemporaryEffectEndTime(
  state: GameState,
  id: string,
  target?: AttackTarget,
): number {
  return getTemporaryEffectEndTimeRaw(state, getLookupId(id, target));
}

export function getTemporaryEffectEndTimeRaw(
  state: GameState,
  fullId: string,
): number {
  return (
    (state.run.temporaryEffects[fullId] as TemporaryEffectData)?.endTime || 0
  );
}

export function getTemporaryEffectSetTime(
  state: GameState,
  id: string,
  target?: AttackTarget,
): number {
  return (
    (
      state.run.temporaryEffects?.[
        getLookupId(id, target)
      ] as TemporaryEffectData
    )?.setTime || 0
  );
}

export function wasTemporaryEffectCleared(
  state: GameState,
  id: string,
  target?: AttackTarget,
): boolean {
  const endTime = getTemporaryEffectEndTimeRaw(state, getLookupId(id, target));
  return (
    endTime < getSecondsPlayed(state) - TEMPORARY_EFFECT_DELTA + CLEAR_EPSILON
  );
}

export function hasTemporaryEffect(
  state: GameState,
  id: string,
  target?: AttackTarget,
): boolean {
  return hasTemporaryEffectRaw(state, getLookupId(id, target));
}

function hasTemporaryEffectRaw(state: GameState, fullId: string): boolean {
  return getTemporaryEffectEndTimeRaw(state, fullId) > getSecondsPlayed(state);
}

export function hasEverHadTemporaryEffect(
  state: GameState,
  id: string,
): boolean {
  return hasWorldFlag(state, "everHadTemporaryEffect-" + id);
}

export function getTemporaryEffectData(
  state: GameState,
  id: string,
  target?: AttackTarget,
  includeEvenIfExpired?: boolean,
): TemporaryEffectData | undefined {
  if (!includeEvenIfExpired && !hasTemporaryEffect(state, id, target)) {
    return undefined;
  }
  return state.run.temporaryEffects[getLookupId(id, target)];
}

export function getTemporaryEffects(
  state: GameState,
  filter?: (state: GameState, effect: TemporaryEffectData) => boolean,
): Array<TemporaryEffectData> {
  let temporaryEffects: TemporaryEffectData[] = [];
  const secondsPlayed = getSecondsPlayed(state);
  for (let effectName in state.run.temporaryEffects) {
    const temporaryEffect = state.run.temporaryEffects[effectName];
    if (
      temporaryEffect.endTime > secondsPlayed &&
      (!filter || filter(state, temporaryEffect))
    ) {
      temporaryEffects.push(temporaryEffect);
    }
  }
  return temporaryEffects;
}

export function getTemporaryEffectsForTarget(
  state: GameState,
  target: AttackTarget,
): Array<TemporaryEffectData> {
  let temporaryEffects: TemporaryEffectData[] = [];
  for (let effectName in state.run.temporaryEffects) {
    if (
      state.run.temporaryEffects[effectName].target == target &&
      hasTemporaryEffectRaw(state, effectName)
    ) {
      const temporaryEffect = state.run.temporaryEffects[effectName];
      temporaryEffects.push(temporaryEffect);
    }
  }
  return temporaryEffects;
}

const temporaryEffectDurationTags = memoize((id, target) => [
  TransformationTags.TemporaryEffectDurationPostProcessing,
  id,
  target,
]);

function grantTemporaryEffectImpl(
  id: string,
  baseDurationSec: number,
  extra: {
    mode?: "replace" | "add";
    target?: AttackTarget;
    params?: Record<string, any>;
  },
  state: GameState,
): GameState {
  const mode = extra.mode ?? "replace";
  const target = extra.target ?? AttackTarget.Player;
  const params = extra.params ?? {};
  const durationSec = applyTransformations(
    temporaryEffectDurationTags(id, target),
    state,
    baseDurationSec,
    { effectExtra: extra },
  );

  const newEndTime =
    baseDurationSec == Infinity
      ? Number.MAX_VALUE / 10
      : getSecondsPlayed(state) + durationSec;
  let temporaryEffectData: TemporaryEffectData = {
    id: id,
    setTime: getSecondsPlayed(state),
    endTime: newEndTime,
    target: target,
    params: params,
    fromAutocast: state.temp.isInAutocast,
    fromPermacast: state.temp.isInPermacast,
    fromPouch: state.temp.isInPouch,
    registeredClear: baseDurationSec < 0,
  };
  if (hasTemporaryEffect(state, id, target)) {
    const temporaryEffect = TemporaryEffects.getById(id);
    temporaryEffectData = temporaryEffect.mergeData(
      state,
      getTemporaryEffectData(state, id, target),
      temporaryEffectData,
      mode,
    );
  }
  for (let listener of Object.values(temporaryEffectListeners)) {
    temporaryEffectData = listener(state, temporaryEffectData);
  }
  state.run.temporaryEffects[getLookupId(id, target)] = temporaryEffectData;
  state = setWorldFlag("everHadTemporaryEffect-" + id)(state);
  return state;
}

export const grantTemporaryEffect = (
  id: string,
  baseDurationSec: number,
  extra: {
    mode?: "replace" | "add";
    target?: AttackTarget;
    params?: Record<string, any>;
  },
) => grantTemporaryEffectImpl.bind(this, id, baseDurationSec, extra);

export function clearTemporaryEffect(
  this: any,
  id: string,
  target?: AttackTarget,
): GameStateTransform {
  return grantTemporaryEffectImpl.bind(this, id, -TEMPORARY_EFFECT_DELTA, {
    target: target,
  });
}

export function clearCombatTemporaryEffects(state: GameState): GameState {
  const effects = getTemporaryEffects(state);
  effects.forEach((effect) => {
    const effectObject = TemporaryEffects.getById(effect.id);
    if (effectObject.isCombatOnly() || effect.target == AttackTarget.Enemy) {
      state = clearTemporaryEffect(effect.id, effect.target)(state);
    }
  });
  return state;
}

export const getTemporaryEffectsForCommonBuff = memoize(
  (stat: CombatStat, type: TransformationType): TemporaryEffect[] => {
    return TemporaryEffects.getAll().filter((effect) =>
      effect
        .isCommonBuffEnabled()
        .some((value) => value.stat == stat && value.type == type),
    );
  },
);

export type TemporaryEffectListenerTransform = (
  state: GameState,
  temporaryEffect: TemporaryEffectData,
) => TemporaryEffectData;

const temporaryEffectListeners: Record<
  string,
  TemporaryEffectListenerTransform
> = {};

export function registerTemporaryEffectListener(
  id: string,
  listener: TemporaryEffectListenerTransform,
  registerExpiration?: boolean,
) {
  temporaryEffectListeners[id] = listener;
  if (registerExpiration) {
    TemporaryEffectExpirationListeners.register(listener);
  }
}

export const TemporaryEffectExpirationListeners = new DataStore<
  (state: GameState, effect: TemporaryEffectData) => void
>();

export function registerTemporaryEffectCombatStatCacheClearer(
  id: string,
  stat: CombatStat,
) {
  registerTemporaryEffectListener(
    id + stat + "cacheClearer",
    (state, effect) => {
      if (effect.id == id) {
        state = clearCombatStatCache(state, effect.target, stat);
      }
      return effect;
    },
    true,
  );
}
