import { t } from "../../../../utils/I18n";
import { incrementAccumulatorVariable } from "../../../AccumulatorVariables";
import { GameState } from "../../../GameState";
import { antidotePotion } from "../../../items/equipment/pouch/AntidotePotion";
import Wildgrass from "../../../items/material/Wildgrass";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { poison } from "../../../temporaryeffects/Poison";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import {
  calculateAttackTarget,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { getLastQueuedActionResult } from "../../ExplorationUtils";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class GoblinSharpshooter extends Enemy {
  getId(): string {
    return "goblinSharpshooter";
  }

  getName(): string {
    return t("Goblin Sharpshooter");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 899,
      attack: 60,
      defense: 25,
      attackDelay: 1.9,
      accuracy: 70,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./goblinSharpshooter.png");
  }

  getLevel(): number {
    return 15;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 68;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 29;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (
      getResourceAmount(state, Resource.Coins) >= 250 &&
      Math.random() < 0.1
    ) {
      return {
        name: t("Steal Coins"),
        delay: this.getAttackDelay(state),
        transform: (state) =>
          grantResource(Resource.Coins, -(50 + 200 * Math.random()))(state),
      };
    }
    if (
      !hasTemporaryEffect(state, poison.getId(), target) &&
      Math.random() < 0.2
    ) {
      return {
        name: t("Poisonous Arrow"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 1.0);
          const lastActionResult = getLastQueuedActionResult(state);
          if (lastActionResult.isHit) {
            state = grantTemporaryEffect(poison.getId(), 15, {
              target: lastActionResult.target,
              params: {
                magnitude: this.getAttack(state) / 19,
              },
            })(state);
          }
          return state;
        },
      };
    }
    if (Math.random() < 0.2) {
      return {
        name: t("Barrage"),
        delay: this.getAttackDelay(state) * 2.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.6);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.6);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.6);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.6);
          return state;
        },
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 2,
        chance: 0.5,
      },
      {
        itemId: cotton.getId(),
        amount: 1,
        chance: 0.25,
      },
      {
        itemId: Wildgrass.getId(),
        amount: 1,
        chance: 0.5,
      },
      {
        itemId: antidotePotion.getId(),
        amount: 1,
        chance: 0.01,
      },
    ];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    if (!isFromCreatures) {
      state = incrementAccumulatorVariable(state, "goblinKills", 1);
    }
    return state;
  }
}

export default new GoblinSharpshooter();
