import React, { useMemo, useState } from "react";
import { StyleSheet, TouchableOpacity, View } from "react-native";
import { Badge, useTheme } from "react-native-paper";
import { getThemeWithOverriddenOnError } from "../../Themes";
import { formatNumber } from "../../utils/FormattingUtils";
import { useAuxOptions } from "../utility/AuxOptionsContext";
import { TooltipWrapper } from "../utility/Tooltip";
import { GameIcon } from "../utility/icons/GameIcon";

const DOUBLE_TAP_THRESHOLD_MS = 500;

function ItemTileNonMemoized(props: {
  onPress?: () => void;
  onDoublePress?: () => void;
  title: string;
  icon?: string;
  amount?: number;
  quality?: number;
  disabled?: boolean;
  tooltipIdSuffix?: string;
  tooltip?: React.ReactNode;
  borderColor?: string;
}) {
  const theme = useTheme();
  const auxOptions = useAuxOptions();
  const [lastTapTime, setLastTapTime] = useState(0);
  const overriddenThemePrimary = getThemeWithOverriddenOnError(
    theme,
    theme.colors.onPrimary,
  );
  const overriddenThemeSecondary = getThemeWithOverriddenOnError(
    theme,
    theme.colors.onSecondary,
  );
  const inner = useMemo(
    () => (
      <View
        style={[
          styles.tile,
          auxOptions.compactLayout && { height: 40, width: 40 },
          { borderColor: props.borderColor ?? theme.colors.outlineVariant },
        ]}
      >
        {props.icon && (
          <GameIcon
            style={props.disabled && { opacity: 0.2 }}
            icon={props.icon}
          />
        )}
        {props.quality != null && props.quality >= 1 && (
          <Badge
            style={{
              position: "absolute",
              opacity: props.disabled ? 0.2 : 1.0,
              bottom: -8,
              left: -8,
              borderColor: theme.colors.background,
              backgroundColor: theme.colors.secondary,
            }}
            theme={overriddenThemeSecondary}
          >
            {"+" + formatNumber(props.quality)}
          </Badge>
        )}
        {props.amount != null && props.amount > 1 && (
          <Badge
            style={{
              position: "absolute",
              top: -8,
              right: -8,
              borderColor: theme.colors.background,
              backgroundColor: theme.colors.primary,
            }}
            theme={overriddenThemePrimary}
          >
            {formatNumber(props.amount)}
          </Badge>
        )}
      </View>
    ),
    [
      theme,
      overriddenThemePrimary,
      overriddenThemeSecondary,
      props.icon,
      props.amount,
      props.disabled,
      props.quality,
      props.borderColor,
      auxOptions.compactLayout,
    ],
  );
  return useMemo(() => {
    if (!props.onPress) {
      return (
        <TooltipWrapper
          containerStyle={[
            styles.tile,
            auxOptions.compactLayout && { height: 40, width: 40 },
            { flex: null, borderColor: theme.colors.outlineVariant },
          ]}
          tooltip={props.tooltip}
          tooltipId={props.title + props.tooltipIdSuffix}
        >
          {inner}
        </TooltipWrapper>
      );
    }
    return (
      <TooltipWrapper
        containerStyle={[
          styles.tile,
          auxOptions.compactLayout && { height: 40, width: 40 },
          { flex: null, borderColor: theme.colors.outlineVariant },
        ]}
        tooltip={props.tooltip}
        tooltipId={props.title + props.tooltipIdSuffix}
      >
        <TouchableOpacity
          accessibilityLabel={
            props.title +
            (props.amount && props.amount > 1 ? ` (${props.amount})` : "")
          }
          accessibilityRole="button"
          onPress={() => {
            const currentTapTime = Date.now();
            if (currentTapTime - lastTapTime < DOUBLE_TAP_THRESHOLD_MS) {
              setLastTapTime(0);
              props.onDoublePress && props.onDoublePress();
            } else {
              setLastTapTime(currentTapTime);
              props.onPress && props.onPress();
            }
          }}
        >
          {inner}
        </TouchableOpacity>
      </TooltipWrapper>
    );
  }, [
    props.title,
    props.tooltip,
    props.onPress,
    props.onDoublePress,
    lastTapTime,
    setLastTapTime,
    inner,
    props.tooltipIdSuffix,
    auxOptions.compactLayout,
  ]);
}

export const ItemTile = React.memo(
  ItemTileNonMemoized,
  (prevProps, nextProps) =>
    prevProps.title == nextProps.title &&
    prevProps.icon == nextProps.icon &&
    prevProps.amount == nextProps.amount &&
    prevProps.quality == nextProps.quality &&
    prevProps.disabled == nextProps.disabled &&
    prevProps.tooltipIdSuffix == nextProps.tooltipIdSuffix &&
    prevProps.onPress == nextProps.onPress &&
    prevProps.onDoublePress == nextProps.onDoublePress &&
    prevProps.tooltip == nextProps.tooltip &&
    prevProps.borderColor == nextProps.borderColor,
);

const styles = StyleSheet.create({
  tile: {
    borderWidth: 0.5,
    height: 48,
    width: 48,
    justifyContent: "center",
    alignItems: "center",
    margin: 8,
  },
});
