import { t } from "i18next";
import mem from "mem";
import { Flag } from "../../Flag";
import { hasEverHadFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { getSecondsPlayed } from "../../timetick/TotalTimePlayed";
import { getAmountOfItem, removeFromInventory } from "../Inventory";
import { Item, ItemAction, ItemParams } from "../Item";
import { ItemTagEnum } from "../ItemTagEnum";
import { Items } from "../Items";
import { TransmutationSpell } from "../transmute/TransmutationSpell";

const memoizedPrimary = mem(
  (
    enabled: boolean,
    active: boolean,
    familiarId: string,
    requireConfirm: boolean,
    id: string,
    params: ItemParams,
  ) => {
    return {
      id: "primaryConsumable",
      description: t("Use"),
      isEnabled: enabled,
      isActive: active,
      requireConfirm: requireConfirm,
      transform: (state: GameState) => {
        // Re-check enabled
        const item = Items.getById(id) as FamiliarItem;
        const refreshedPrimary = item.getFamiliarAction(
          state,
          params,
          familiarId,
        );
        if (refreshedPrimary?.isEnabled) {
          state = removeFromInventory({ itemId: id, params }, 1, state);
          state.run.lastItemUsedTimes[id] = getSecondsPlayed(state);
          state = (Items.getById(id) as FamiliarItem).doFamiliarItemEffect(
            state,
            params,
            familiarId,
          );
        }
        return state;
      },
    };
  },
  { cacheKey: JSON.stringify },
);

/**
 * A consumable item that requires a familiar.
 */
export abstract class FamiliarItem extends Item {
  abstract doFamiliarItemEffect(
    state: GameState,
    params: ItemParams,
    familiarId: string,
  ): GameState;

  canUseOnFamiliar(
    state: GameState,
    params: ItemParams,
    familiarId: string,
  ): boolean {
    return true;
  }

  getFamiliarAction(
    state: GameState,
    params: ItemParams,
    familiarId: string,
  ): ItemAction | undefined {
    const numberInInventory = getAmountOfItem(
      { itemId: this.getId(), params },
      state,
    );
    const active = this.isActive(state);
    const enabled =
      numberInInventory > 0 && this.canUseOnFamiliar(state, params, familiarId);
    return memoizedPrimary(
      enabled,
      active,
      familiarId,
      this.shouldConfirm(),
      this.getId(),
      params,
    );
  }

  getTags(): ItemTagEnum[] {
    return [ItemTagEnum.Familiar];
  }

  shouldConfirm(): boolean {
    return false;
  }
}

export abstract class FamiliarTransmutationSpell extends TransmutationSpell {
  abstract getItem(): FamiliarItem;

  isVisible(state: GameState): boolean {
    return (
      hasEverHadFlag(state, Flag.FamiliarsUnlocked) && super.isVisible(state)
    );
  }
}
