import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import {
  getElementExp,
  getElementLevel,
  getPrimaryElements,
  getTotalExpRequiredForLevel,
  getUnlockedElements,
} from "../../backend/spells/Elements";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

function ElementRow(props: {
  name: string;
  level: number;
  isPrimary: boolean;
  currentExp: string;
  toNextLevel: string;
}) {
  return (
    <View style={styles.row} accessible={true}>
      <Text
        doNotScaleFonts={true}
        style={props.isPrimary && styles.primaryText}
      >
        {t("{{name}}: Lv{{level}}", { name: props.name, level: props.level })}
      </Text>
      <Text
        doNotScaleFonts={true}
        style={props.isPrimary && styles.primaryText}
      >
        {t("Exp {{currentExp}}/{{nextLevelExp}}", {
          currentExp: props.currentExp,
          nextLevelExp: props.toNextLevel,
        })}
      </Text>
    </View>
  );
}

const SchoolRowMemo = React.memo(ElementRow);

export function ElementViewer(props: { isVisible: boolean }) {
  const state = useGameState();
  const keys = getUnlockedElements(state);
  const rows = useMemo(
    () =>
      keys.map((key) => {
        const level = getElementLevel(state, key);
        const currentExp = getElementExp(state, key);
        const toNextLevel = getTotalExpRequiredForLevel(state, level + 1, key);
        return (
          <SchoolRowMemo
            name={key}
            level={level}
            isPrimary={getPrimaryElements(state).includes(key)}
            currentExp={formatNumber(currentExp)}
            toNextLevel={formatNumber(toNextLevel)}
            key={key}
          />
        );
      }),
    [
      props.isVisible !== false
        ? JSON.stringify(state.run.elementExperience)
        : false,
      getPrimaryElements(state).join(),
    ],
  );
  return <View style={styles.container}>{rows}</View>;
}

const styles = StyleSheet.create({
  container: {
    justifyContent: "center",
    alignItems: "stretch",
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-end",
  },
  primaryText: {
    fontWeight: "bold",
  },
});
