import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { Menu } from "react-native-paper";
import { memoizeWithLimit } from "sonic-memoize";
import { disableAllAutosold } from "../../backend/autocast/Market";
import { isMarketUnlocked } from "../../backend/buildings/SaleStall";
import {
  getAmountOfItem,
  getItemStackFromInventory,
  isItemOccurrenceEqual,
} from "../../backend/items/Inventory";
import {
  ItemOccurrence,
  ItemTag,
  translateItemTag,
} from "../../backend/items/Item";
import { ItemTagEnum } from "../../backend/items/ItemTagEnum";
import {
  Items,
  combineAll,
  isItemQualityUnlocked,
  sellLowQualityItems,
} from "../../backend/items/Items";
import { t } from "../../utils/I18n";
import { ITEM_TAG_ORDER } from "../Transmute";
import { Button } from "../utility/Button";
import { Picker as UtilityPicker } from "../utility/Picker";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { UIContext, useUIStateFromContext } from "../utility/UIContext";
import { VisibilityContext } from "../utility/VisibilityContext";
import { useGameState } from "../utility/useGameState";
import { ItemDetails } from "./ItemDetails";
import { ItemGrid, ItemGridElement } from "./ItemGrid";

const itemFilterer = (selectedTag, itemStack) =>
  selectedTag === "All" ||
  Items.getById(itemStack.itemOccurrence.itemId)
    .getTags()
    .includes(selectedTag);

const itemSorter = (a, b) => {
  const itemA = Items.getById(a.itemOccurrence.itemId);
  const itemB = Items.getById(b.itemOccurrence.itemId);
  for (let tag of ITEM_TAG_ORDER) {
    if (itemA.getTags().includes(tag) && !itemB.getTags().includes(tag)) {
      return -1;
    }
    if (itemB.getTags().includes(tag) && !itemA.getTags().includes(tag)) {
      return 1;
    }
  }
  if (itemA == itemB) {
    return (
      (b.itemOccurrence.params.itemQuality ?? 0) -
      (a.itemOccurrence.params.itemQuality ?? 0)
    );
  }
  return (
    Items.getNumericId(a.itemOccurrence.itemId) -
    Items.getNumericId(b.itemOccurrence.itemId)
  );
};

const getItemGridElement = memoizeWithLimit(
  (itemId, amount, params, disableTooltip) => ({
    itemOrSpellId: itemId,
    amount: amount,
    params: JSON.parse(params),
    disableTooltip,
  }),
  10000,
);

export default function InventoryItems(props: {
  disableSelling?: boolean;
  showQuickbar?: boolean;
  showExtraActions?: boolean;
  isFullBleed?: boolean;
  overrideMaxHeight?: number;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const uiContext = useContext(UIContext);
  const [selectedTagString, setSelectedTag] = useUIStateFromContext(
    uiContext,
    "InventoryTag",
    "All",
  );
  const selectedTag = selectedTagString as ItemTagEnum | "All";
  const [selectedItem, setSelectedItem] = useState(
    null as ItemOccurrence | null,
  );
  const inventory = state.run.inventory;

  const tagSet = new Set<string>();
  inventory.forEach((itemStack) => {
    const tags = Items.getById(itemStack.itemOccurrence.itemId).getTags();
    tags.forEach((tag) => tagSet.add(tag));
  });
  const tags = Array.from(tagSet).sort((a, b) => {
    const indexA = ITEM_TAG_ORDER.findIndex((tag) => tag == a);
    const indexB = ITEM_TAG_ORDER.findIndex((tag) => tag == b);
    return indexA - indexB;
  });
  useEffect(() => {
    if (!tags.includes(selectedTag)) {
      setSelectedTag("All");
    }
  }, [tags.join(), selectedTag]);

  const tagButtons = useMemo(() => {
    return (
      <UtilityPicker
        onValueChange={setSelectedTag}
        selectedValue={selectedTag}
        data={["All", ...tags].map((tag) => ({
          label: tag == "All" ? t("All") : translateItemTag(tag as ItemTag),
          value: tag,
          key: tag,
        }))}
        disablePlaceholder={true}
      />
    );
  }, [setSelectedTag, selectedTag, JSON.stringify(tags)]);

  const sortedInventory = inventory
    .filter(itemFilterer.bind(null, selectedTag))
    .sort(itemSorter);

  useEffect(() => {
    if (selectedItem != null) {
      const paramsJSON = JSON.stringify(selectedItem.params);
      if (
        !sortedInventory.some(
          (itemStack) =>
            itemStack.itemOccurrence.itemId == selectedItem.itemId &&
            JSON.stringify(itemStack.itemOccurrence.params) == paramsJSON,
        )
      ) {
        setSelectedItem(null);
      }
    }
  }, [selectedTag]);

  const itemGridElements = sortedInventory.map((itemStack, index) => {
    return getItemGridElement(
      itemStack.itemOccurrence.itemId,
      itemStack.amount,
      JSON.stringify(itemStack.itemOccurrence.params),
      isItemOccurrenceEqual(itemStack.itemOccurrence, selectedItem),
    );
  });

  const visible = useContext(VisibilityContext);
  useEffect(() => {
    if (!visible) {
      setSelectedItem(null);
    }
  }, [visible]);

  useEffect(() => {
    if (visible && selectedItem != null) {
      if (getAmountOfItem(selectedItem, state) <= 0) {
        setSelectedItem(null);
      }
    }
  }, [selectedItem, visible, state]);

  const itemDetails = selectedItem != null &&
    getItemStackFromInventory(state, selectedItem) != null && (
      <ItemDetails
        disableSelling={props.disableSelling}
        showQuickbar={props.showQuickbar}
        itemStack={getItemStackFromInventory(state, selectedItem)}
        selectItem={setSelectedItem}
      />
    );

  const sortedInventoryRef = useRef(sortedInventory);
  sortedInventoryRef.current = sortedInventory;

  const onPressItem = React.useCallback(
    (item: ItemGridElement) => {
      setSelectedItem({ itemId: item.itemOrSpellId, params: item.params });
    },
    [setSelectedItem],
  );

  const onDoublePressItem = React.useCallback((item: ItemGridElement) => {
    stateRef.current.apply((state) => {
      const selectedItem = { itemId: item.itemOrSpellId, params: item.params };
      if (selectedItem?.itemId != null) {
        const item = Items.getById(selectedItem.itemId);
        const primaryAction = item?.getPrimaryAction(
          state,
          selectedItem.params,
        );
        if (primaryAction != null && primaryAction?.isEnabled) {
          const transform = primaryAction.transform;
          return transform != null ? transform(state) : state;
        }
      }
      return state;
    });
  }, []);

  const itemGrid = useMemo(
    () => (
      <ItemGrid
        items={itemGridElements}
        onPressItem={onPressItem}
        onDoublePressItem={onDoublePressItem}
        overrideMaxHeight={props.overrideMaxHeight}
      />
    ),
    [
      JSON.stringify(itemGridElements),
      onPressItem,
      onDoublePressItem,
      props.overrideMaxHeight,
    ],
  );

  const [menuVisible, setMenuVisible] = useState(false);
  const toggleMenuVisible = useCallback(
    () => setMenuVisible(!menuVisible),
    [menuVisible, setMenuVisible],
  );

  const menuButton = useMemo(
    () => (
      <Button compact={true} onPress={toggleMenuVisible}>
        {t("More actions...")}
      </Button>
    ),
    [toggleMenuVisible],
  );
  const combineAllAction = useMemo(
    () => (
      <Menu.Item
        key="combineAll"
        title={t("Combine all")}
        onPress={() => {
          stateRef.current.apply(combineAll);
          setMenuVisible(false);
        }}
      />
    ),
    [setMenuVisible],
  );
  const sellLowQualityAction = useMemo(
    () => (
      <Menu.Item
        key="sellLowQuality"
        title={t("Sell low quality items")}
        onPress={() => {
          stateRef.current.apply(sellLowQualityItems);
          setMenuVisible(false);
        }}
      />
    ),
    [setMenuVisible],
  );
  const removeAllFromSaleStallsAction = useMemo(
    () => (
      <Menu.Item
        key="removeAllFromSaleStalls"
        title={t("Clear market stall marks")}
        onPress={() => {
          stateRef.current.apply(disableAllAutosold);
          setMenuVisible(false);
        }}
      />
    ),
    [setMenuVisible],
  );
  const menuDismiss = useCallback(
    () => setMenuVisible(false),
    [setMenuVisible],
  );
  const menuActions = useMemo(() => {
    const menuActions = [];
    if (isItemQualityUnlocked(state)) {
      menuActions.push(combineAllAction);
      menuActions.push(sellLowQualityAction);
    }
    if (isMarketUnlocked(state)) {
      menuActions.push(removeAllFromSaleStallsAction);
    }
    return menuActions;
  }, [combineAllAction, sellLowQualityAction, isItemQualityUnlocked(state)]);

  const menu = useMemo(
    () =>
      menuActions.length > 0 &&
      props.showExtraActions && (
        <Menu visible={menuVisible} anchor={menuButton} onDismiss={menuDismiss}>
          {menuActions}
        </Menu>
      ),
    [props.showExtraActions, menuActions, menuVisible, menuButton, menuDismiss],
  );

  return useMemo(() => {
    if (inventory.length == 0) {
      return (
        <View style={Styles.p8}>
          <Text style={{ fontStyle: "italic" }}>
            {t("You are not holding any items yet!")}
          </Text>
        </View>
      );
    }

    return (
      <View>
        <View style={[Styles.mb8, Styles.ml8, Styles.flexRowSpaceBetween]}>
          <View style={styles.tagPickerContainer}>{tagButtons}</View>
          {menu}
        </View>
        {itemGrid}
        <View
          style={[
            styles.detailsContainer,
            props.isFullBleed && styles.detailsContainerPadding,
          ]}
        >
          {itemDetails}
        </View>
      </View>
    );
  }, [
    inventory.length,
    tagButtons,
    menu,
    itemGrid,
    itemDetails,
    props.isFullBleed,
  ]);
}

const styles = StyleSheet.create({
  detailsContainer: {
    marginTop: 18,
  },
  detailsContainerPadding: {
    paddingHorizontal: 8,
    paddingBottom: 8,
  },
  tagPickerContainer: {
    width: 180,
    ...Platform.select({
      android: { backgroundColor: "#eee", borderRadius: 10, default: {} },
    }),
  },
});
