import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import SlimeDrop from "../../../items/material/SlimeDrop";
import Wildgrass from "../../../items/material/Wildgrass";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { noxiousCloak } from "../../../temporaryeffects/NoxiousCloak";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class EvolvedSlime extends Enemy {
  getId(): string {
    return "evolvedSlime";
  }

  getName(): string {
    return t("Evolved Slime");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 550,
      attack: 32,
      defense: 10,
      attackDelay: 2.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./evolvedSlime.png");
  }

  getLevel(): number {
    return 9;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 48;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 15;
  }

  getNextAction(state: GameState): BattlerAction {
    if (!hasTemporaryEffect(state, noxiousCloak.getId(), AttackTarget.Enemy)) {
      return {
        name: t("Noxious Cloak"),
        delay: this.getAttackDelay(state),
        transform: (state) =>
          grantTemporaryEffect(noxiousCloak.getId(), 60, {
            target: AttackTarget.Enemy,
            params: {
              magnitude: this.getAttack(state) / 8,
              duration: 1,
            },
          })(state),
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: SlimeDrop.getId(),
        amount: 1,
        chance: 1.0,
      },
      {
        itemId: Wildgrass.getId(),
        amount: 1,
        chance: 0.35,
      },
      {
        itemId: copperOre.getId(),
        amount: 1,
        chance: 0.4,
      },
    ];
  }
}

export default new EvolvedSlime();
