import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { sewageSystem } from "../../buildings/production/SewageSystem";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { clearCalculatedIncomeCache } from "../../timetick/IncomeOverTime";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";
import { sewageSystemEnhancement } from "../other/SewageSystem";

class ImprovedPurification extends Enhancement {
  getId(): string {
    return "improvedPurification";
  }
  getBaseName(): string {
    return t("Improved Purification");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_PRODUCTION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 15000,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 20,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [sewageSystemEnhancement];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Rearrange the Sewage System workings, so that they can produce much more Essence, although at a much lower effectivity.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t(
      "2.5x essence production; 5x :poisonessence: consumption of Sewage System",
    );
  }
}

const improvedPurification = new ImprovedPurification();

registerTransformation(
  [[sewageSystem.getId(), TransformationTags.Production]],
  improvedPurification.getId(),
  improvedPurification.getBaseName(),
  TransformationType.Multiplier,
  (state) => (improvedPurification.isPurchased(state) ? 2.5 : 1.0),
);

registerTransformation(
  [[sewageSystem.getId(), TransformationTags.Consumption]],
  improvedPurification.getId() + "consumption",
  improvedPurification.getBaseName(),
  TransformationType.Multiplier,
  (state) => (improvedPurification.isPurchased(state) ? 5.0 : 1.0),
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == improvedPurification) {
    state = clearCalculatedIncomeCache(state);
  }
  return state;
});

export { improvedPurification };
