import { t } from "../../../../utils/I18n";
import { incrementAccumulatorVariable } from "../../../AccumulatorVariables";
import { GameState } from "../../../GameState";
import Wildgrass from "../../../items/material/Wildgrass";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { SpellElement } from "../../../spells/Elements";
import { poison } from "../../../temporaryeffects/Poison";
import { Seared } from "../../../temporaryeffects/Seared";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import {
  calculateAttackTarget,
  getCurrentEnemyHPFraction,
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { pushCombatActionResult } from "../../ExplorationActionResult";
import { getLastQueuedActionResult } from "../../ExplorationUtils";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class GoblinDoctor extends Enemy {
  getId(): string {
    return "goblinDoctor";
  }

  getName(): string {
    return t("Goblin Doctor");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 1199,
      attack: 60,
      defense: 25,
      attackDelay: 2.3,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./goblinDoctor.png");
  }

  getLevel(): number {
    return 18;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 62;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 45;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (
      getResourceAmount(state, Resource.Coins) >= 250 &&
      Math.random() < 0.1
    ) {
      return {
        name: t("Steal Coins"),
        delay: this.getAttackDelay(state),
        transform: (state) =>
          grantResource(Resource.Coins, -(50 + 200 * Math.random()))(state),
      };
    }
    if (
      !hasTemporaryEffect(state, poison.getId(), target) &&
      Math.random() < 0.2
    ) {
      return {
        name: t("Sting"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.0,
            { tags: [SpellElement.Poison] },
          );
          const lastActionResult = getLastQueuedActionResult(state);
          if (lastActionResult.isHit) {
            state = grantTemporaryEffect(poison.getId(), 12, {
              target: lastActionResult.target,
              params: {
                magnitude: this.getAttack(state) / 16,
              },
            })(state);
          }
          return state;
        },
      };
    }
    if (Math.random() < 0.1) {
      return {
        name: t("Sear"),
        delay: this.getAttackDelay(state) * 2.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 2.0,
            { tags: [SpellElement.Fire] },
          );
          const lastActionResult = getLastQueuedActionResult(state);
          if (lastActionResult.isHit) {
            state = grantTemporaryEffect(Seared.getId(), 12, {
              target: lastActionResult.target,
            })(state);
          }
          return state;
        },
      };
    }
    if (
      hasTemporaryEffect(state, Seared.getId(), target) &&
      Math.random() < 0.8
    ) {
      return {
        name: t("Air Blast"),
        delay: this.getAttackDelay(state) * 0.5,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 0.7,
            { tags: [SpellElement.Air] },
          );
          return state;
        },
      };
    }
    if (Math.random() < 0.2) {
      return {
        name: t("Fireball"),
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.5,
            { tags: [SpellElement.Fire] },
          );
          return state;
        },
      };
    }
    if (Math.random() < 0.2 && getCurrentEnemyHPFraction(state) < 1.0) {
      return {
        name: t("Droplet of Healing"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          const healAmt = this.getMaxHP(state) * (0.05 + Math.random() * 0.025);
          state = modifyEnemyCurrentHP(state, healAmt);
          const actionResult = {
            id: Math.random().toString(),
            damage: -healAmt,
            isCrit: false,
            isHit: true,
            time: getSecondsPlayed(state),
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
          };
          state = pushCombatActionResult(state, actionResult);
          return state;
        },
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 2,
        chance: 1.0,
      },
      {
        itemId: cotton.getId(),
        amount: 2,
        chance: 0.2,
      },
      {
        itemId: Wildgrass.getId(),
        amount: 1,
        chance: 0.8,
      },
    ];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    if (!isFromCreatures) {
      state = incrementAccumulatorVariable(state, "goblinKills", 1);
    }
    return state;
  }
}

export default new GoblinDoctor();
