import { compressedEssence } from "./storage/CompressedEssence";
import { primaryStorage } from "./storage/PrimaryStorage";
import { wideStorage } from "./storage/WideStorage";

export function loadEnhancementsDemo() {
  loadEnhancementCommonLogic();

  // Storage
  registerEnhancement(compressedEssence);
  registerEnhancement(treasureChests);
  registerEnhancement(primaryStorage);
  registerEnhancement(wideStorage);

  // Land
  registerEnhancement(landTreaty);
  registerEnhancement(houseExpansionContract);
  registerEnhancement(furnitureOrganization);

  // Production
  registerEnhancement(superheatedCatalysts);
  registerEnhancement(sewageSystemEnhancement);
  registerEnhancement(improvedPurification);
  registerEnhancement(superheatedSteamers);

  // Exploration
  registerEnhancement(efficientExploration);
  registerEnhancement(healthBoost);
  registerEnhancement(healthPotionEffectivity);
  registerEnhancement(firstAid);
  registerEnhancement(monstiumCollection);
  registerEnhancement(enlargedPouch);
  registerEnhancement(compass);
  registerEnhancement(doubleHealthBoost);
  registerEnhancement(professionalPouch);
  registerEnhancement(tripleHealthBoost);
  registerEnhancement(giantPouch);

  // Other
  registerEnhancement(understandingOfMagic);
  registerEnhancement(deeperUnderstandingOfMagic);
  registerEnhancement(manaSteamers);
  registerEnhancement(glitteringStalls);
  registerEnhancement(monstiumCatalysts);
  registerEnhancement(hiringSpree);
  registerEnhancement(scalableChambers);
  registerEnhancement(shardDiligence);
}
