import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { Chilled } from "../../../temporaryeffects/Chilled";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { Wet } from "../../../temporaryeffects/Wet";
import { AttackTarget } from "../../AttackTarget";
import {
  getEnemyTurnCounter,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class Sicklefish extends Enemy {
  getId(): string {
    return "sicklefish";
  }

  getName(): string {
    return t("Sicklefish");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 900,
      attack: 52,
      defense: 30,
      attackDelay: 2.1,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./sicklefish.png");
  }

  getLevel(): number {
    return 15;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 54;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 31;
  }

  getNextAction(state: GameState): BattlerAction {
    if (getEnemyTurnCounter(state) % 4 == 0) {
      return {
        name: t("Strong Attack"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 2.0);
          return state;
        },
      };
    }
    if (getEnemyTurnCounter(state) % 4 == 3) {
      return {
        name: t("Charging..."),
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state) => state,
      };
    }
    if (
      !hasTemporaryEffect(state, Wet.getId(), AttackTarget.Enemy) &&
      !hasTemporaryEffect(state, Chilled.getId(), AttackTarget.Enemy)
    ) {
      return {
        name: t("Soak"),
        delay: this.getAttackDelay(state) * 0.2,
        transform: (state) =>
          grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Enemy,
          })(state),
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 4,
        chance: 0.2,
      },
      {
        itemId: cotton.getId(),
        amount: 1,
        chance: 0.3,
      },
    ];
  }
}

export default new Sicklefish();
