import { MaterialIcons } from "@expo/vector-icons";
import AsyncStorage from "@react-native-async-storage/async-storage";
import * as Clipboard from "expo-clipboard";
import React, {
  RefObject,
  useCallback,
  useMemo,
  useRef,
  useState,
} from "react";
import {
  Platform,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
  useWindowDimensions,
} from "react-native";
import DraggableFlatList, {
  DragEndParams,
  RenderItemParams,
  ScaleDecorator,
} from "react-native-draggable-flatlist";
import { TextInput, useTheme } from "react-native-paper";
import { Flag } from "../backend/Flag";
import { hasFlag } from "../backend/Flags";
import { GameStateValue, INITIAL_STATE } from "../backend/GameState";
import { serializeSaveData } from "../backend/SaveData";
import { EventTag } from "../backend/events/GameEvent";
import {
  SpellElement,
  getFullyUnlockedElements,
  translateElement,
} from "../backend/spells/Elements";
import { clone } from "../utils/CoreUtils";
import {
  LOAD_FROM_FILE_SUPPORTED,
  SAVE_TO_FILE_SUPPORTED,
  loadTranslationsFromFile,
  saveToFile,
} from "../utils/FilesystemSave";
import { formatNumber } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import { Storage } from "../utils/Storage";
import { ZOOM_SUPPORTED } from "../utils/Zoom";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import { CheckBox } from "./utility/CheckBox";
import LoadGameContainer from "./utility/LoadGameContainer";
import {
  DEFAULT_SECTION_ORDER,
  getAllMenuItems,
  getSectionInfo,
} from "./utility/Miscellaneous";
import { Picker as UtilityPicker } from "./utility/Picker";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { GameIcon } from "./utility/icons/GameIcon";
import { useGameState } from "./utility/useGameState";

const FPS_DATA = ["10", "12", "15", "20", "24", "30", "40", "50", "60"].map(
  (x) => ({ label: x, key: x, value: x }),
);

const ZOOM_DATA = [
  0.5, 0.6, 0.7, 0.8, 0.85, 0.9, 0.95, 0.98, 1.0, 1.02, 1.05, 1.1, 1.151, 1.2,
  1.25, 1.3, 1.4, 1.5, 1.6, 1.7, 1.8, 1.9, 2.0, 2.25, 2.5, 2.75, 3.0,
].map((x) => ({
  label: formatNumber(x, { showDecimals: true }) + "x",
  key: x.toString(),
  value: x.toString(),
}));

function useEventTagToggle(stateRef: RefObject<GameStateValue>, tag: EventTag) {
  return useCallback(() => {
    stateRef.current?.apply((oldState) => {
      if (oldState.options.hideEventTags.includes(tag)) {
        oldState.options.hideEventTags.splice(
          oldState.options.hideEventTags.findIndex(
            (innerTag) => innerTag === tag,
          ),
          1,
        );
      } else {
        oldState.options.hideEventTags.push(tag);
      }
      return oldState;
    });
  }, []);
}

function useHiddenFromBottomMenuToggle(
  stateRef: RefObject<GameStateValue>,
  section: string,
) {
  return useCallback(() => {
    stateRef.current?.apply((oldState) => {
      if (oldState.options.hiddenFromBottomMenu.includes(section)) {
        oldState.options.hiddenFromBottomMenu.splice(
          oldState.options.hiddenFromBottomMenu.findIndex(
            (innerSection) => innerSection === section,
          ),
          1,
        );
      } else {
        oldState.options.hiddenFromBottomMenu.push(section);
      }
      return oldState;
    });
  }, []);
}

function BottomMenuToggle(props: { section: string }) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const onPress = useHiddenFromBottomMenuToggle(stateRef, props.section);

  const title = useMemo(
    () => (
      <View style={styles.toggleMenuRow}>
        <GameIcon icon={props.section.toLowerCase()} small />
        <View style={styles.toggleMenuTextContainer}>
          <Text>{getSectionInfo(props.section).name}</Text>
        </View>
      </View>
    ),
    [props.section],
  );

  return (
    <View style={[Styles.mt8, Styles.mr8]}>
      <CheckBox
        title={title}
        checked={!state.options.hiddenFromBottomMenu.includes(props.section)}
        onPress={onPress}
        containerStyle={styles.toggleMenuCheckboxContainer}
      />
    </View>
  );
}

function MenuSection(props: { drag: () => void; section: string }) {
  const state = useGameState();
  const theme = useTheme();
  const sectionInfo = getSectionInfo(props.section);

  const visible =
    sectionInfo == null ? false : sectionInfo?.isVisible?.(state) ?? true;
  const title = sectionInfo?.name;

  return useMemo(
    () =>
      visible && (
        <ScaleDecorator>
          <View style={Styles.flexRowAlignCenter}>
            <View
              importantForAccessibility="no-hide-descendants"
              accessibilityElementsHidden={true}
              style={styles.dragHandle}
            >
              <TouchableWithoutFeedback
                onPressIn={props.drag}
                hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
                importantForAccessibility="no-hide-descendants"
                accessibilityElementsHidden={true}
              >
                <MaterialIcons
                  name="drag-indicator"
                  size={24}
                  color={theme.colors.onSecondaryContainer}
                />
              </TouchableWithoutFeedback>
            </View>
            <View style={[Styles.flex, Styles.flexRowAlignCenter]}>
              {sectionInfo != null && (
                <GameIcon icon={props.section.toLowerCase()} />
              )}
              <View style={[Styles.flex, Styles.ml16]}>
                <Text>{title}</Text>
              </View>
            </View>
          </View>
        </ScaleDecorator>
      ),
    [props.drag, theme, title, visible],
  );
}

function MenuSectionsOrder(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const renderItem = useCallback(
    ({ item, getIndex, drag, isActive }: RenderItemParams<string>) => (
      <MenuSection drag={drag} section={item} />
    ),
    [],
  );
  const keyExtractor = useCallback((section: string) => section, []);
  const onDragEnd = useCallback((params: DragEndParams<string>) => {
    stateRef.current.apply((state) => {
      state.options.sectionOrder = params.data;
      return state;
    });
  }, []);

  const data =
    state.options.sectionOrder.length > 0
      ? state.options.sectionOrder
      : DEFAULT_SECTION_ORDER;

  return (
    <DraggableFlatList
      renderItem={renderItem}
      data={data}
      keyExtractor={keyExtractor}
      onDragEnd={onDragEnd}
      nestedScrollEnabled={true}
    />
  );
}

export default function Options() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const [copyToClipboardError, setCopyToClipboardError] = useState(null);

  const copyToClipboard = useCallback(async () => {
    const serializedData = await serializeSaveData(stateRef.current);
    await Clipboard.setStringAsync(serializedData);

    if (Platform.OS == "android" || Platform.OS == "ios") {
      const savedData = await Clipboard.getStringAsync();
      if (savedData != serializedData) {
        setCopyToClipboardError(
          t("Warning: save did not copy to clipboard correctly!"),
        );
      } else {
        setCopyToClipboardError(null);
      }
    }
  }, []);

  const targetFpsChange = useCallback((value: string) => {
    stateRef.current.apply((oldState) => {
      oldState.options.targetFps = Number.parseInt(value);
      return oldState;
    });
  }, []);

  const zoomChange = useCallback((value: string) => {
    stateRef.current.apply((oldState) => {
      oldState.options.zoomLevel = Number.parseFloat(value);
      return oldState;
    });
  }, []);

  const elementOverrideChange = useCallback((value: string) => {
    stateRef.current.apply((oldState) => {
      oldState.options.overrideElementDisplay = value as SpellElement | "no";
      return oldState;
    });
  }, []);

  const elementOptions = [
    {
      label: t("Primary Element"),
      key: "no",
      value: "no",
    },
    ...getFullyUnlockedElements(state).map((x) => ({
      label: translateElement(x),
      key: x,
      value: x,
    })),
  ];

  const hideBottomResourceViewerToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.hideBottomResourceViewer =
        !oldState.options.hideBottomResourceViewer;
      return oldState;
    });
  }, []);

  const hideChannelingSpellsFromHomeToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.hideChannelingSpellsOnHome =
        !oldState.options.hideChannelingSpellsOnHome;
      return oldState;
    });
  }, []);

  const hideDamageEffectsToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.hideDamageEffects = !oldState.options.hideDamageEffects;
      return oldState;
    });
  }, []);

  const doNotUseNativeDriverToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.doNotUseNativeDriver =
        !oldState.options.doNotUseNativeDriver;
      return oldState;
    });
  }, []);

  const darkModeToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.themeMode =
        oldState.options.themeMode === "dark" ? "light" : "dark";
      return oldState;
    });
  }, []);

  const colorSpellToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.colorSpellButtonsByElement =
        !oldState.options.colorSpellButtonsByElement;
      return oldState;
    });
  }, []);

  const compactUIToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.compactUI = !oldState.options.compactUI;
      return oldState;
    });
  }, []);

  const expandedUIToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.saveExpandedUI = !oldState.options.saveExpandedUI;
      return oldState;
    });
  }, []);

  const hideTooltipsToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.hideTooltips = !oldState.options.hideTooltips;
      return oldState;
    });
  }, []);

  const wakeLockCallback = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.wakeLock = !oldState.options.wakeLock;
      return oldState;
    });
  }, []);

  const enableBottomMenuCallback = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.enableBottomMenu = !oldState.options.enableBottomMenu;
      return oldState;
    });
  }, []);

  const leftHandedModeCallback = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.leftHandedMode = !oldState.options.leftHandedMode;
      return oldState;
    });
  }, []);

  const putCastSpellOnBottomMenuCallback = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.putCastSpellOnBottomMenu =
        !oldState.options.putCastSpellOnBottomMenu;
      return oldState;
    });
  }, []);

  const doNotShowEventsCallback = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.doNotShowEventsOnWideScreens =
        !oldState.options.doNotShowEventsOnWideScreens;
      return oldState;
    });
  }, []);

  const eventTagLevelUpAlreadyReachedToggle = useEventTagToggle(
    stateRef,
    EventTag.LEVEL_UP_ALREADY_REACHED,
  );
  const eventTagLevelUpEmptyToggle = useEventTagToggle(
    stateRef,
    EventTag.LEVEL_UP_EMPTY,
  );
  const eventTagLevelUpAllToggle = useEventTagToggle(
    stateRef,
    EventTag.LEVEL_UP,
  );
  const eventTagInconsequentialRandomEventsToggle = useEventTagToggle(
    stateRef,
    EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
  );
  const eventTagMainQuestSeenBeforeEventsToggle = useEventTagToggle(
    stateRef,
    EventTag.MAIN_QUEST_SEEN_BEFORE,
  );
  const eventTagBossFightToggle = useCallback(() => {
    stateRef.current.apply((oldState) => {
      oldState.options.doNotPopupEventsDuringBossFights =
        !oldState.options.doNotPopupEventsDuringBossFights;
      return oldState;
    });
  }, []);

  const [eraseText, setEraseText] = useState("");
  const eraseGame = useCallback(() => {
    stateRef.current.overwrite((state) => {
      return clone(INITIAL_STATE);
    });
    AsyncStorage.removeItem("MR2.GameState");
    AsyncStorage.removeItem("MR2.LastSaveSecondsPlayed");
  }, []);

  const theme = useTheme();

  const loadText = LOAD_FROM_FILE_SUPPORTED
    ? t(
        "You may import save data by loading it from a file, or by pasting it here.",
      )
    : t("You may import save data by pasting it here.");

  const saveToFileCallback = useCallback(async () => {
    const result = await saveToFile(stateRef.current);
  }, []);

  const hasTranslations = Storage.getString("uploadedTranslationsJSON") != null;

  const [translationMessage, setTranslationMessage] = useState("");

  const importTranslationFileCallback = useCallback(async () => {
    try {
      const result = await loadTranslationsFromFile();
      if (result.result === "error") {
        setTranslationMessage(
          t(
            "There was an error reading from the file. Is it a valid translation file? {{error}}",
            { error: result.error },
          ),
        );
      }
      if (result.result === "success") {
        Storage.set("uploadedTranslationsJSON", JSON.stringify(result.data));
        setTranslationMessage(
          t(
            "Successfully loaded translations file! Restart the app for it to take effect.",
          ),
        );
      }
    } catch (e) {
      setTranslationMessage(
        t(
          "There was an error reading from the file. Is it a valid translation file?",
        ),
      );
    }
  }, [setTranslationMessage]);

  const clearTranslationFileCallback = useCallback(() => {
    Storage.delete("uploadedTranslationsJSON");
    // Intentionally not translating the following line
    setTranslationMessage(
      "Successfully deleted translations! Restart the app for it to take effect.",
    );
  }, [setTranslationMessage]);

  const { width, height } = useWindowDimensions();

  const bottomMenuToggles = useMemo(
    () =>
      getAllMenuItems(state).map((section) => (
        <BottomMenuToggle key={section} section={section} />
      )),
    [getAllMenuItems(state).join()],
  );

  const labelColor = useMemo(
    () => ({ color: theme.colors.onBackground }),
    [theme],
  );

  const performanceCategory = useMemo(() => {
    return (
      <CategoryContainer title="Performance">
        <View style={styles.categoryContainer}>
          <Text>
            {t(
              `You may set your target UI updates per second (UUPS) here. Higher UUPS will make the game elements refresh quicker but it could affect battery power and CPU usage.`,
            )}
          </Text>
          <View style={[Styles.mt8, Styles.flexRowAlignCenter]}>
            <Text style={labelColor}>{t("Target UUPS:")}</Text>
            <View style={styles.targetFpsDropdownContainer}>
              <UtilityPicker
                onValueChange={targetFpsChange}
                selectedValue={state.options.targetFps.toString()}
                style={styles.targetFpsPicker}
                data={FPS_DATA}
              />
            </View>
          </View>
          <View style={styles.hideDamageEffectsContainer}>
            <Text>
              {t(
                "The following options may help improve performance in various screens.",
              )}
            </Text>
            <View>
              <View>
                <CheckBox
                  title={t("Hide bottom resource viewer")}
                  checked={state.options.hideBottomResourceViewer}
                  onPress={hideBottomResourceViewerToggle}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
              <View>
                <CheckBox
                  title={t("Hide channeling spells on Home screen")}
                  checked={state.options.hideChannelingSpellsOnHome}
                  onPress={hideChannelingSpellsFromHomeToggle}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            </View>
          </View>
          {hasFlag(state, Flag.ExplorationUnlocked) && (
            <View style={styles.hideDamageEffectsContainer}>
              <Text>
                {t(
                  "If you are seeing performance problems specifically in the Exploration screen, you may turn off damage effects here, which should improve performance somewhat. The alternate animation code for the bars is less smooth, and may or may not improve performance; you may want to try both.",
                )}
              </Text>
              <View>
                <View>
                  <CheckBox
                    title={t("Hide damage effects")}
                    checked={state.options.hideDamageEffects}
                    onPress={hideDamageEffectsToggle}
                    containerStyle={styles.checkboxContainer}
                  />
                </View>
                <View>
                  <CheckBox
                    title={t("Use alternate animation code for the bars")}
                    checked={state.options.doNotUseNativeDriver}
                    onPress={doNotUseNativeDriverToggle}
                    containerStyle={styles.checkboxContainer}
                  />
                </View>
              </View>
            </View>
          )}
        </View>
      </CategoryContainer>
    );
  }, [
    hideBottomResourceViewerToggle,
    state.options.hideBottomResourceViewer,
    hideChannelingSpellsFromHomeToggle,
    state.options.hideChannelingSpellsOnHome,
    doNotUseNativeDriverToggle,
    state.options.doNotUseNativeDriver,
    hideDamageEffectsToggle,
    state.options.hideDamageEffects,
    hasFlag(state, Flag.ExplorationUnlocked),
    targetFpsChange,
    state.options.targetFps.toString(),
    FPS_DATA,
    labelColor,
  ]);

  const keepAwakeCategory = useMemo(
    () => (
      <CategoryContainer title={t("Keep Awake")}>
        <View style={styles.categoryContainer}>
          <Text>
            {t(
              `By default, devices go to sleep after a period of inactivity, to save battery. You can disable this behavior while you are playing Magic Research actively in the foreground.`,
            )}
          </Text>
          <CheckBox
            title={t("Prevent device from sleeping")}
            checked={state.options.wakeLock}
            onPress={wakeLockCallback}
            containerStyle={Styles.mt8}
          />
        </View>
      </CategoryContainer>
    ),
    [state.options.wakeLock, wakeLockCallback],
  );

  const hideEventPopupsCategory = useMemo(
    () => (
      <CategoryContainer title="Hide Event Popups">
        <View style={styles.categoryContainer}>
          <Text>
            {t(
              "Hide certain kinds of events, so that they don't disrupt gameplay. Hidden events will not show a popup, but will still be accessible through the Event Menu.",
            )}
          </Text>
          <View style={styles.eventPopupsContainer}>
            {state.world.totalTimesResetted > 0 && (
              <View>
                <CheckBox
                  title={t(
                    "Hide element level up events of levels that you have reached in previous retirements",
                  )}
                  checked={state.options.hideEventTags.includes(
                    EventTag.LEVEL_UP_ALREADY_REACHED,
                  )}
                  onPress={eventTagLevelUpAlreadyReachedToggle}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            )}
            <View>
              <CheckBox
                title={t(
                  "Hide element level up events if nothing new is unlocked",
                )}
                checked={state.options.hideEventTags.includes(
                  EventTag.LEVEL_UP_EMPTY,
                )}
                onPress={eventTagLevelUpEmptyToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title={t("Hide all element level up events")}
                checked={state.options.hideEventTags.includes(
                  EventTag.LEVEL_UP,
                )}
                onPress={eventTagLevelUpAllToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            {state.world.totalTimesResetted > 0 && (
              <View>
                <CheckBox
                  title={t(
                    "Hide and skip main progression events that require no action and that you have seen in previous retirements",
                  )}
                  checked={state.options.hideEventTags.includes(
                    EventTag.MAIN_QUEST_SEEN_BEFORE,
                  )}
                  onPress={eventTagMainQuestSeenBeforeEventsToggle}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            )}
            <View>
              <CheckBox
                title={t(
                  "Hide common random events (random events which are not Storylines and which appear more than once)",
                )}
                checked={state.options.hideEventTags.includes(
                  EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
                )}
                onPress={eventTagInconsequentialRandomEventsToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title={t(
                  "Hide events when you are in the middle of a boss fight",
                )}
                checked={state.options.doNotPopupEventsDuringBossFights}
                onPress={eventTagBossFightToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
          </View>
        </View>
      </CategoryContainer>
    ),
    [
      state.options.hideEventTags.join(),
      eventTagInconsequentialRandomEventsToggle,
      eventTagLevelUpAllToggle,
      eventTagLevelUpEmptyToggle,
      eventTagLevelUpAlreadyReachedToggle,
      eventTagMainQuestSeenBeforeEventsToggle,
      state.options.doNotPopupEventsDuringBossFights,
      eventTagBossFightToggle,
      state.world.totalTimesResetted,
    ],
  );

  const bottomMenuCategory = useMemo(
    () =>
      width < 700 && (
        <CategoryContainer title={t("Bottom Menu")}>
          <View style={styles.categoryContainer}>
            <Text>
              {t(
                "In smaller screens, there is a menu at the bottom to quickly switch between screens. You can customize the menu in this section. If you are left handed, you can swap the position of the Cast Spell button to be able to use it more easily. If you prefer to have the extra screen space instead, you can also disable this menu here.",
              )}
            </Text>
            <View>
              <CheckBox
                title={t("Enable bottom menu")}
                checked={state.options.enableBottomMenu}
                onPress={enableBottomMenuCallback}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title={t("Show Cast Spell button in bottom menu")}
                checked={state.options.putCastSpellOnBottomMenu}
                onPress={putCastSpellOnBottomMenuCallback}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View>
              <CheckBox
                title={t("Left handed mode")}
                checked={state.options.leftHandedMode}
                onPress={leftHandedModeCallback}
                containerStyle={styles.checkboxContainer}
              />
            </View>
            <View style={Styles.mt16}>
              <Text>
                {t(
                  "You can also customize which screens will show up on the bottom menu. You will always be able to navigate to these screens by using the main menu on the left hand side. The bottom menu will show the following screens:",
                )}
              </Text>
              <View style={styles.hideBottomMenuContainer}>
                {bottomMenuToggles}
              </View>
            </View>
            <View style={Styles.mt16}>
              <Text>
                {t(
                  "In addition, you may re-order the screens in the bottom menu expanding the following:",
                )}
              </Text>
              <CategoryContainer
                title={t("Re-order Bottom Menu Items")}
                initiallyExpanded={false}
              >
                <View style={Styles.mt8}>
                  <MenuSectionsOrder />
                </View>
              </CategoryContainer>
            </View>
          </View>
        </CategoryContainer>
      ),
    [
      width,
      state.options.enableBottomMenu,
      state.options.putCastSpellOnBottomMenu,
      state.options.leftHandedMode,
      enableBottomMenuCallback,
      putCastSpellOnBottomMenuCallback,
      leftHandedModeCallback,
      bottomMenuToggles,
    ],
  );

  const themingAndUICategory = useMemo(
    () => (
      <CategoryContainer title="Theming and UI">
        <View style={styles.categoryContainer}>
          <Text>{t("Change the colors to be easier on the eyes.")}</Text>
          <View>
            <CheckBox
              title={t("Enable dark mode")}
              checked={state.options.themeMode === "dark"}
              onPress={darkModeToggle}
              containerStyle={styles.checkboxContainer}
            />
          </View>
          <View style={Styles.mt16}>
            <Text>
              {t(
                "The game remembers whether you had certain UI elements expanded or not, so that when you open the game again, they stay in the same status. You can turn off this functionality here.",
              )}
            </Text>
            <View>
              <CheckBox
                title={t("Remember expanded / collapsed UI status")}
                checked={state.options.saveExpandedUI}
                onPress={expandedUIToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
          </View>
          {Platform.OS === "web" && (
            <View style={Styles.mt16}>
              <Text>
                {t("You can disable tooltips if they get in the way.")}
              </Text>
              <View>
                <CheckBox
                  title={t("Disable tooltips")}
                  checked={state.options.hideTooltips}
                  onPress={hideTooltipsToggle}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            </View>
          )}
          {width >= 1350 && (
            <View style={Styles.mt16}>
              <Text>
                {t(
                  "If you have a wide screen, you can make the Events menu collapsible in order to get more out of your screen space.",
                )}
              </Text>
              <View>
                <CheckBox
                  title={t("Collapse Events menu on the right")}
                  checked={state.options.doNotShowEventsOnWideScreens}
                  onPress={doNotShowEventsCallback}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            </View>
          )}
          <View style={Styles.mt16}>
            <Text>
              {t(
                "By default, the game changes the colors of the buttons and interface depending on the chosen element. If you'd like, you can instead choose to always show buttons with the looks of the element of your choice.",
              )}
            </Text>
            <View style={[Styles.mt8, Styles.flexRowAlignCenter]}>
              <Text style={labelColor}>{t("Game UI color:")}</Text>
              <View style={styles.targetFpsDropdownContainer}>
                <UtilityPicker
                  onValueChange={elementOverrideChange}
                  selectedValue={state.options.overrideElementDisplay}
                  style={styles.uiColorPicker}
                  disablePlaceholder={true}
                  data={elementOptions}
                />
              </View>
            </View>
          </View>
          <View style={Styles.mt16}>
            <Text>
              {t(
                "Buttons to cast spells are colored by the spell's Element by default, but you can turn off this behavior.",
              )}
            </Text>
            <View>
              <CheckBox
                title={t("Color spell buttons by Element")}
                checked={state.options.colorSpellButtonsByElement}
                onPress={colorSpellToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
          </View>
          <View style={Styles.mt16}>
            <Text>
              {t(
                "In very small screens, you can opt for an extra-compact UI to see more on the screen, at the expense of buttons being harder to press. Not recommended for large screens. {{extra}}",
                {
                  extra:
                    Platform.OS != "web"
                      ? t(
                          "Changing the device's font size (making it smaller) might also help somewhat.",
                        )
                      : "",
                },
              )}
            </Text>
            <View>
              <CheckBox
                title={t("Compact UI (Experimental)")}
                checked={state.options.compactUI}
                onPress={compactUIToggle}
                containerStyle={styles.checkboxContainer}
              />
            </View>
          </View>
          {ZOOM_SUPPORTED && (
            <View style={styles.elementOverrideContainer}>
              <Text>
                {t("You can also adjust the zoom level on the game.")}
              </Text>
              <View style={[Styles.mt8, Styles.flexRowAlignCenter]}>
                <Text style={labelColor}>{t("Zoom scale:")}</Text>
                <View style={styles.targetFpsDropdownContainer}>
                  <UtilityPicker
                    onValueChange={zoomChange}
                    selectedValue={state.options.zoomLevel.toString()}
                    style={styles.uiColorPicker}
                    data={ZOOM_DATA}
                  />
                </View>
              </View>
            </View>
          )}
        </View>
      </CategoryContainer>
    ),
    [
      state.options.themeMode,
      darkModeToggle,
      state.options.saveExpandedUI,
      expandedUIToggle,
      labelColor,
      state.options.overrideElementDisplay,
      elementOverrideChange,
      state.options.colorSpellButtonsByElement,
      colorSpellToggle,
      state.options.doNotShowEventsOnWideScreens,
      doNotShowEventsCallback,
      state.options.compactUI,
      compactUIToggle,
      ZOOM_SUPPORTED,
      state.options.zoomLevel,
      zoomChange,
      ZOOM_DATA,
      state.options.hideTooltips,
      hideTooltipsToggle,
      width,
    ],
  );

  const saveFileCategory = useMemo(
    () => (
      <CategoryContainer title={t("Save File")}>
        <View style={styles.categoryContainer}>
          <Text>
            {t(
              "You may persist your save data by copying it to the clipboard and storing it anywhere you'd like.",
            )}
          </Text>
          <Button onPress={copyToClipboard} style={Styles.mt8}>
            {t("Copy savefile to clipboard")}
          </Button>
          {copyToClipboardError != null && (
            <View style={Styles.mt8}>
              <Text style={{ color: theme.colors.error }}>
                {copyToClipboardError}
              </Text>
            </View>
          )}
        </View>
        {SAVE_TO_FILE_SUPPORTED && (
          <View style={styles.categoryContainer}>
            <Text>
              {t(
                "You can also save your data directly to a file or elsewhere.",
              )}
            </Text>
            <Button onPress={saveToFileCallback} style={Styles.mt8}>
              {t("Save to file")}
            </Button>
          </View>
        )}
      </CategoryContainer>
    ),
    [
      copyToClipboard,
      SAVE_TO_FILE_SUPPORTED,
      saveToFileCallback,
      copyToClipboardError,
      theme,
    ],
  );

  const loadFileCategory = useMemo(
    () => (
      <CategoryContainer title={t("Load File")}>
        <View style={styles.categoryContainer}>
          <Text>{loadText}</Text>
          <Text style={{ fontWeight: "bold", color: theme.colors.error }}>
            {t(
              `WARNING: This will erase your current save! Make sure you have backed up your data!`,
            )}
          </Text>
          <LoadGameContainer />
        </View>
      </CategoryContainer>
    ),
    [theme.colors.error, loadText],
  );

  const languageCategory = useMemo(
    () =>
      LOAD_FROM_FILE_SUPPORTED && (
        <CategoryContainer title={t("Language")}>
          <View style={styles.categoryContainer}>
            <Text>
              {t(
                "Although the game does not provide official translations, if you would rather play in a different language, the game supports loading a translations file which will replace the text in the game. If you have such a file, you can load it below.",
              )}
            </Text>
            <Button onPress={importTranslationFileCallback} style={Styles.mt8}>
              {t("Load translations file")}
            </Button>
            {hasTranslations && (
              <Button onPress={clearTranslationFileCallback} style={Styles.mt8}>
                {t("Clear translations")}
              </Button>
            )}
            {translationMessage != "" && (
              <Text style={{ color: theme.colors.error }}>
                {translationMessage}
              </Text>
            )}
          </View>
        </CategoryContainer>
      ),
    [
      theme.colors.error,
      translationMessage,
      LOAD_FROM_FILE_SUPPORTED,
      importTranslationFileCallback,
    ],
  );

  const eraseSavedDataCategory = useMemo(
    () => (
      <CategoryContainer title="Erase Saved Data">
        <View style={styles.categoryContainer}>
          <Text>
            {t(
              `If you want to start all over again and retirement isn't enough, you can delete your save data entirely.`,
            )}
          </Text>
          <Text style={{ fontWeight: "bold", color: theme.colors.error }}>
            {t(
              `WARNING: This will erase your current save, including all your progress with Storylines and retirement! Make sure you have backed up your data! Once you have done this, there is no going back! If you are sure you want to do this, write "erase" in the text field below.`,
            )}
          </Text>
          <TextInput
            style={Styles.mt16}
            value={eraseText}
            onChangeText={setEraseText}
          />
          <View style={Styles.mt8}>
            <Button
              disabled={eraseText.toLowerCase() != "erase"}
              buttonColor={theme.colors.errorContainer}
              textColor={theme.colors.onErrorContainer}
              onPress={eraseGame}
            >
              {t("Permanently erase all saved data")}
            </Button>
          </View>
        </View>
      </CategoryContainer>
    ),
    [
      eraseText,
      eraseGame,
      setEraseText,
      eraseText,
      theme.colors.error,
      theme.colors.errorContainer,
    ],
  );

  return useMemo(
    () => (
      <View style={styles.container}>
        {performanceCategory}
        {keepAwakeCategory}
        {hideEventPopupsCategory}
        {bottomMenuCategory}
        {themingAndUICategory}
        {saveFileCategory}
        {loadFileCategory}
        {languageCategory}
        {eraseSavedDataCategory}
      </View>
    ),
    [
      performanceCategory,
      keepAwakeCategory,
      hideEventPopupsCategory,
      bottomMenuCategory,
      themingAndUICategory,
      saveFileCategory,
      loadFileCategory,
      languageCategory,
      eraseSavedDataCategory,
    ],
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  headerContainer: {
    marginBottom: 16,
  },
  categoryContainer: {
    marginTop: 8,
    paddingHorizontal: 8,
  },
  eventPopupsContainer: {
    marginTop: 8,
  },
  targetFpsDropdownContainer: {
    ...Platform.select({
      android: { backgroundColor: "#eee", borderRadius: 10, default: {} },
    }),
    marginLeft: 16,
  },
  targetFpsPicker: {
    width: 100,
  },
  uiColorPicker: {
    width: 160,
  },
  sliderContainer: {
    width: 200,
    marginLeft: 16,
  },
  sliderTextContainer: {
    width: 120,
  },
  checkboxContainer: {
    marginLeft: 0,
    marginTop: 8,
    padding: 0,
  },
  checkboxText: {
    fontWeight: "normal",
  },
  saveButton: {
    marginTop: 8,
  },
  loadMessageContainer: {
    marginBottom: 8,
  },
  inputContainer: {
    borderWidth: 1,
    marginTop: 16,
    maxHeight: 48,
  },
  toggleMenuCheckboxContainer: {
    padding: 0,
    marginLeft: 0,
  },
  toggleMenuRow: {
    flexDirection: "row",
    alignItems: "center",
  },
  hideBottomMenuContainer: {
    flexDirection: "row",
    flexWrap: "wrap",
  },
  toggleMenuTextContainer: {
    paddingLeft: 4,
  },
  bottomMenuCustomizationContainer: {
    marginTop: 8,
  },
  hideDamageEffectsContainer: {
    marginTop: 8,
  },
  elementOverrideContainer: {
    marginTop: 8,
  },
  dragHandle: {
    cursor: "grab",
    marginRight: 8,
  },
});
