import { clone } from "../../utils/CoreUtils";
import { setFlag } from "../Flags";
import { GameState, GameStateTransform } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { DataStore } from "../generic/DataStore";
import { Item, ItemOccurrence, ItemStack } from "./Item";
import { isInventoryUnlocked, Items } from "./Items";
import { ItemTagEnum } from "./ItemTagEnum";

function getItemOccurrenceIndex(
  itemOccurrence: ItemOccurrence,
  state: GameState,
): number {
  const itemOccurrenceParamsJSON = JSON.stringify(itemOccurrence.params);
  return state.run.inventory.findIndex((itemStack) => {
    return (
      itemStack.itemOccurrence.itemId == itemOccurrence.itemId &&
      itemOccurrenceParamsJSON ==
        JSON.stringify(itemStack.itemOccurrence.params)
    );
  });
}

export function isItemOccurrenceEqual(
  itemOccurrenceA: ItemOccurrence,
  itemOccurrenceB: ItemOccurrence,
) {
  return (
    itemOccurrenceA?.itemId == itemOccurrenceB?.itemId &&
    JSON.stringify(itemOccurrenceA?.params) ==
      JSON.stringify(itemOccurrenceB?.params)
  );
}

export function getItemStackFromInventory(
  state: GameState,
  itemOccurrence: ItemOccurrence,
): ItemStack | null {
  const index = getItemOccurrenceIndex(itemOccurrence, state);
  if (index === -1) {
    return null;
  }
  return state.run.inventory[index];
}

export const AddToInventoryForFirstTimeListeners =
  new DataStore<GameStateTransform>();

export function addToInventory(
  itemOccurrence: ItemOccurrence,
  amount: number,
  state: GameState,
): GameState {
  if (!state.world.itemsEverAddedToInventory[itemOccurrence.itemId]) {
    state.world.itemsEverAddedToInventory[itemOccurrence.itemId] = true;
    AddToInventoryForFirstTimeListeners.getAll().forEach((transform) => {
      state = transform(state);
    });
  }
  const previousIndex = getItemOccurrenceIndex(itemOccurrence, state);

  if (previousIndex >= 0) {
    state.run.inventory[previousIndex] = {
      itemOccurrence,
      amount: state.run.inventory[previousIndex].amount + amount,
    };
    return state;
  }

  state.run.inventory.push({
    itemOccurrence,
    amount,
  });

  return state;
}

export function removeFromInventory(
  itemOccurrence: ItemOccurrence,
  amount: number,
  state: GameState,
): GameState {
  const previousIndex = getItemOccurrenceIndex(itemOccurrence, state);
  if (previousIndex < 0) {
    return state;
  }

  const newAmount = state.run.inventory[previousIndex].amount - amount;
  if (newAmount > 0) {
    state.run.inventory[previousIndex] = {
      itemOccurrence,
      amount: newAmount,
    };
  } else {
    state.run.inventory.splice(previousIndex, 1);
  }
  return state;
}

export function getAmountOfItem(
  itemOccurrence: ItemOccurrence,
  state: GameState,
): number {
  const index = getItemOccurrenceIndex(itemOccurrence, state);
  if (index === -1) {
    return 0;
  }
  return state.run.inventory[index].amount;
}

export function getTotalAmountOfItem(item: Item, state: GameState): number {
  return state.run.inventory
    .filter((itemStack) => itemStack.itemOccurrence.itemId == item.getId())
    .reduce((count, current) => count + current.amount, 0);
}

export function getTotalAmountOfItemWithMinimumQuality(
  item: Item,
  minimumQuality: number,
  state: GameState,
): number {
  return state.run.inventory
    .filter(
      (itemStack) =>
        itemStack.itemOccurrence.itemId == item.getId() &&
        (itemStack.itemOccurrence.params.itemQuality ?? 0) >= minimumQuality,
    )
    .reduce((count, current) => count + current.amount, 0);
}

export function removeFromInventoryWithAnyParams(
  item: Item,
  amount: number,
  state: GameState,
): GameState {
  const oldRunState = state.run.inventory;
  state.run.inventory = clone(state.run.inventory);

  while (amount > 0) {
    const inventory = state.run.inventory;
    const firstIndex = inventory.findIndex(
      (itemStack) => itemStack.itemOccurrence.itemId == item.getId(),
    );
    if (firstIndex < 0) {
      // Abort
      state.run.inventory = oldRunState;
      return state;
    }
    const itemAmount = inventory[firstIndex].amount;
    state = removeFromInventory(
      inventory[firstIndex].itemOccurrence,
      Math.min(itemAmount, amount),
      state,
    );
    amount -= itemAmount;
  }

  return state;
}

export function removeFromInventoryWithMinimumQuality(
  item: Item,
  amount: number,
  minimumQuality: number,
  state: GameState,
): GameState {
  const oldRunState = state.run.inventory;
  state.run.inventory = clone(state.run.inventory);

  while (amount > 0) {
    const matchingItems = state.run.inventory.filter(
      (itemStack) =>
        itemStack.itemOccurrence.itemId == item.getId() &&
        (itemStack.itemOccurrence.params.itemQuality ?? 0) >= minimumQuality,
    );
    const sortedByQuality = matchingItems.sort(
      (a, b) =>
        (a.itemOccurrence.params.itemQuality ?? 0) -
        (b.itemOccurrence.params.itemQuality ?? 0),
    );
    if (sortedByQuality.length <= 0) {
      // Abort
      state.run.inventory = oldRunState;
      return state;
    }
    const itemAmount = sortedByQuality[0].amount;
    state = removeFromInventory(
      sortedByQuality[0].itemOccurrence,
      Math.min(itemAmount, amount),
      state,
    );
    amount -= itemAmount;
  }

  return state;
}

// Place items with those tags in order
const ITEM_TAG_ORDER = ["Consumable", "Hand", "Body", "Accessory"];
// Then if two items have the same tags from the above list, put those who
// have this tag in the back of that sub-category
const ITEM_TAG_LAST_ORDER = ["Material"];

function hasTag(
  itemOccurrence: ItemOccurrence,
  state: GameState,
  tag: string,
): boolean {
  const item = Items.getById(itemOccurrence.itemId);
  return item.getTags().includes(tag as ItemTagEnum);
}

export function sortInventory(state: GameState): GameState {
  state.run.inventory.sort((a, b) => {
    for (let tag of ITEM_TAG_ORDER) {
      const aHasTag = hasTag(a.itemOccurrence, state, tag);
      const bHasTag = hasTag(b.itemOccurrence, state, tag);
      if (aHasTag && !bHasTag) {
        return -1;
      }
      if (bHasTag && !aHasTag) {
        return 1;
      }
    }
    for (let tag of ITEM_TAG_LAST_ORDER) {
      const aHasTag = hasTag(a.itemOccurrence, state, tag);
      const bHasTag = hasTag(b.itemOccurrence, state, tag);
      if (aHasTag && !bHasTag) {
        return 1;
      }
      if (bHasTag && !aHasTag) {
        return -1;
      }
    }
    return 0;
  });
  return state;
}

export function loadInventoryListener() {
  addGameStateListener("InventoryUnlockerListener", (newState) => {
    if (newState.run.inventory.length > 0 && !isInventoryUnlocked(newState)) {
      return setFlag("new_inventory_unlocked");
    }
  });
}
