import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { DataStore } from "../generic/DataStore";
import { triggerNotification } from "../Notifications";

export type Help = {
  id: string;
  category: string;
  title: string;
  description: string;
  descriptionWeb?: string;
  order: number;
  isVisible?: (state: GameState) => boolean;
};

export enum HelpCategory {
  Introduction = "Introduction",
  ScreensOverview = "ScreensOverview",
  ElementsOverview = "ElementsOverview",
  GameFeatures = "GameFeatures",
  Wizards = "Wizards",
  Exploration = "Exploration",
  TipsAndTricks = "TipsAndTricks",
}

export function translateHelpCategory(helpCategory: HelpCategory): string {
  switch (helpCategory) {
    case HelpCategory.Introduction:
      return t("Introduction");
    case HelpCategory.ScreensOverview:
      return t("Screens Overview");
    case HelpCategory.ElementsOverview:
      return t("Elements Overview");
    case HelpCategory.GameFeatures:
      return t("Game Features");
    case HelpCategory.Wizards:
      return t("Wizards");
    case HelpCategory.Exploration:
      return t("Exploration");
    case HelpCategory.TipsAndTricks:
      return t("Tips and Tricks");
    // Do not add a default
  }
}

const Helps = new DataStore<Help>();

export { Helps };

export function getVisibleHelps(state: GameState): Help[] {
  return Helps.getAll()
    .filter((help) => help.isVisible == null || help.isVisible(state))
    .sort((a, b) => a.order - b.order);
}

export function getVisibleHelpsByCategory(
  state: GameState,
): Record<HelpCategory, Help[]> {
  const helps = getVisibleHelps(state);
  const helpsByCategory = {
    Introduction: [],
    ScreensOverview: [],
    ElementsOverview: [],
    GameFeatures: [],
    Wizards: [],
    Exploration: [],
    TipsAndTricks: [],
  };
  for (let help of helps) {
    helpsByCategory[help.category] = helpsByCategory[help.category] ?? [];
    helpsByCategory[help.category].push(help);
  }
  for (let category in helpsByCategory) {
    if (helpsByCategory[category].length == 0) {
      delete helpsByCategory[category];
    }
  }
  return helpsByCategory;
}

let tempIds: string | undefined = undefined;

addGameStateListener(
  "HelpNotifier",
  (state) => {
    if (tempIds == null) {
      return;
    }

    const oldIds = tempIds?.split(",") ?? [];
    const newHelps = getVisibleHelps(state);
    const idsToNotify = [];
    for (let help of newHelps) {
      if (!oldIds.includes(help.order.toString())) {
        idsToNotify.push(help.id);
      }
    }
    if (idsToNotify.length > 0) {
      return (newState) => {
        for (let helpId of idsToNotify) {
          newState = triggerNotification(newState, "Help", helpId);
        }
        return newState;
      };
    }
  },
  (state, oldValue) => {
    tempIds = oldValue as string | undefined;
    return getVisibleHelps(state)
      .map((help) => help.order.toString())
      .join(",");
  },
);
