import { t } from "../../../../utils/I18n";
import { FLOOR_FOR_ELEMENT } from "../../../exploration/dungeons/training/TrainingTowers";
import {
  addEnemyDeathListener,
  getSuccessfulExplorationsForFloor,
} from "../../../exploration/Exploration";
import {
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../../../items/Inventory";
import { Items } from "../../../items/Items";
import { RESOURCE_FOR_ELEMENT } from "../../../spells/channeling/ChannelingResources";
import { SpellElement, translateElement } from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  IncomeOverTimeProducer,
  IncomeOverTimeProducers,
} from "../../../timetick/IncomeOverTime";
import { getOrCreateDungeonEventGroup } from "../../DungeonEventGroup";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

function createStorylineForElement(element: SpellElement) {
  return new Storyline(
    "essenceArtifact" + element,
    t("Essence Artifact ({{element}})", { element: translateElement(element) }),
    100 + enabledElements.indexOf(element),
    10,
    (state) =>
      getOrCreateDungeonEventGroup("trainingTowers")
        .getSelectedEvent(state)
        ?.getId() ==
      "essenceArtifact" + element
        ? t("Explore the Tower of {{element}} Training at least 60 times", {
            element: translateElement(element),
          })
        : t(
            "This Storyline can be found in the Training Towers, but you cannot get it this run",
          ),
    t("+100{{essenceIcon}}/sec", {
      essenceIcon: `:${RESOURCE_FOR_ELEMENT[element].toLowerCase()}:`,
    }),
    (_state) => true,
  );
}

const enabledElements = [
  SpellElement.Fire,
  SpellElement.Earth,
  SpellElement.Water,
  SpellElement.Air,
];

const essenceArtifactStorylines: Partial<Record<SpellElement, Storyline>> =
  Object.fromEntries(
    enabledElements.map((element) => [
      element,
      createStorylineForElement(element),
    ]),
  );

export function loadEssenceArtifact() {
  const dungeonEventGroup = getOrCreateDungeonEventGroup("trainingTowers");
  for (let element of enabledElements) {
    Storylines.register(essenceArtifactStorylines[element]);

    const essenceArtifactEventBuilder = buildEvent(
      "essenceArtifact" + element,
      t("(Storyline) Essence Artifact ({{element}})", {
        element: translateElement(element),
      }),
      [EventTag.STORYLINE],
    ).setStorylineIds(essenceArtifactStorylines[element].getId());

    essenceArtifactEventBuilder.message(
      t(
        `Exploring the Tower of {{element}} Training, you find a strange small cauldron-like object. You find it odd, and haven't seen it before. You decide to bring it home to study it further.`,
        { element: translateElement(element) },
      ),
    );
    essenceArtifactEventBuilder
      .message(
        t(
          `At home, you examine the object some more. It looks like an artifact of some sort. It is decorated with what you learned as the traditional symbols of {{element}}.
    
You notice a small drawer / opening on the bottom. It appears like there are countless slots for... *something*. It seems like they are about the same size of an elemental shard. Perhaps you should try filling it up...?`,
          { element: translateElement(element) },
        ),
      )
      .option(
        t("Insert elemental shards (100 {{element}} Shard)", {
          element: translateElement(element),
        }),
        {
          transform: (state) => {
            state = removeFromInventoryWithAnyParams(
              Items.getById("elementalShard" + element),
              100,
              state,
            );
            return state;
          },
          isEnabled: (state) => {
            return (
              getTotalAmountOfItem(
                Items.getById("elementalShard" + element),
                state,
              ) >= 100
            );
          },
        },
        "end",
      )
      .option(t("Maybe later"))
      .end();
    essenceArtifactEventBuilder
      .message(
        t(
          `You insert all the shards into the compartment, and align them one by one diligently. You then close the compartment, and wait. But nothing happens. Did you do something wrong?
        
You turn around the object, and notice an inscription on the back side. How did you miss this? It says: *"{{element}} Essence Materializer"*. It sounds exciting. But then you notice more: *"Charge Time: 30 Years"*...`,
          { element: translateElement(element) },
        ),
      )
      .option(
        "Continue",
        {
          transform: (state) =>
            essenceArtifactStorylines[element].complete(state),
        },
        "final",
      )
      .tag("end")
      .end();
    essenceArtifactEventBuilder
      .message(
        t(
          `*What?* You will need to wait *30 years* for this to work? Changing your mind, you open the compartment to retrieve your shards. Or at least you *try* to - it appears to be locked! Even pulling strongly on it doesn't open it. It's as if it was fully sealed.
          
You sigh deeply. Perhaps you shouldn't be taking random artifacts from places and playing with them. You quickly perish the thought - what if any of those were hints on how to obtain the Philosopher's Stone? Regardless, you put away the artifact, resigned that you won't be able to do anything with it...

**You have completed the "Essence Artifact ({{element}})" Storyline! In future runs, you will gain a permanent +100{{essenceIcon}}/sec!**`,
          {
            element: translateElement(element),
            essenceIcon:
              ":" + RESOURCE_FOR_ELEMENT[element].toLowerCase() + ":",
          },
        ),
      )
      .tag("final");

    const essenceArtifactEvent = essenceArtifactEventBuilder.build();
    const dungeonFloor = FLOOR_FOR_ELEMENT[element];

    addEnemyDeathListener(
      essenceArtifactEvent.getId() + "Triggerer",
      (state) => {
        const selectedEvent = dungeonEventGroup.getSelectedEvent(state);
        if (
          selectedEvent == essenceArtifactEvent &&
          !essenceArtifactStorylines[element].isCompleted(state) &&
          !essenceArtifactEvent.hasTriggered(state) &&
          getSuccessfulExplorationsForFloor(state, dungeonFloor.getId()) >= 60
        ) {
          return triggerEvent(essenceArtifactEvent)(state);
        }
        return state;
      },
    );

    IncomeOverTimeProducers.register(
      new IncomeOverTimeProducer(
        "incomeOverTime" + element,
        essenceArtifactStorylines[element].getName(),
        (state) => {
          const bonus = {};
          if (essenceArtifactStorylines[element].isBonusActive(state)) {
            bonus[RESOURCE_FOR_ELEMENT[element]] = 100;
          }
          return bonus;
        },
      ),
    );

    dungeonEventGroup.addEvent({
      event: essenceArtifactEvent,
      getWeight: (state) => {
        if (essenceArtifactStorylines[element].isCompleted(state)) {
          return 0;
        }
        return 100;
      },
    });
  }
}

export { essenceArtifactStorylines };
