import { formatMultiplier } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { ActionEffect } from "../../../action/Action";
import {
  buildSewageSystem,
  sewageSystem,
} from "../../../buildings/production/SewageSystem";
import { SpellElement } from "../../../spells/Elements";
import { clearCalculatedIncomeCache } from "../../../timetick/IncomeOverTime";
import {
  TransformationType,
  registerTransformation,
} from "../../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../../transformation/TransformationTags";
import { SynchroChangeListeners } from "../../Synchro";
import { SynchroBonuses } from "../../SynchroBonuses";
import { WaterSynchroBonus } from "../BaseSynchroBonuses";

class WaterPoisonSynchroBonus extends WaterSynchroBonus {
  getElement2(): SpellElement {
    return SpellElement.Poison;
  }

  isVisible(state: GameState): boolean {
    return buildSewageSystem.isVisible(state);
  }

  protected getBaseBonusEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 0.5,
        valueType: TransformationValueType.Percent,
      },
      magnitudeConsumption: {
        value: 1.0,
        valueType: TransformationValueType.Percent,
      },
    };
  }

  getEffect(state: GameState, amount: number): string {
    const magnitude = this.getBonusEffect(state, "magnitude", amount);
    const magnitudeConsumption = this.getBonusEffect(
      state,
      "magnitudeConsumption",
      amount,
    );
    const explanations = this.getBonusEffectExplanations(state, amount);
    return t(
      "^{{magnitude}}^<{{explanation}}> production, but ^{{magnitudeConsumption}}^<{{explanationConsumption}}> consumption of Sewage Systems",
      {
        magnitude: formatMultiplier(magnitude),
        explanation: explanations.magnitude,
        magnitudeConsumption: formatMultiplier(magnitudeConsumption),
        explanationConsumption: explanations.magnitudeConsumption,
      },
    );
  }
}

export function loadWaterPoisonSynchroBonus() {
  const bonus = new WaterPoisonSynchroBonus();
  SynchroBonuses.register(bonus);
  registerTransformation(
    [[sewageSystem.getId(), TransformationTags.Production]],
    bonus.getId(),
    bonus.getName(),
    TransformationType.Multiplier,
    (state) => {
      return bonus.getCurrentBonusEffect(state, "magnitude");
    },
  );
  registerTransformation(
    [[sewageSystem.getId(), TransformationTags.Consumption]],
    bonus.getId(),
    bonus.getName(),
    TransformationType.Multiplier,
    (state) => {
      return bonus.getCurrentBonusEffect(state, "magnitudeConsumption");
    },
  );
  SynchroChangeListeners.register(
    (state) => clearCalculatedIncomeCache(state),
    bonus.getId(),
  );
}
