import React, { useMemo, useRef } from "react";
import {
  Platform,
  ScrollView,
  StyleSheet,
  View,
  useWindowDimensions,
} from "react-native";
import { useTheme } from "react-native-paper";
import { ItemCompendiumInfo } from "../../backend/items/ItemCompendium";
import { Items } from "../../backend/items/Items";
import { TransmutationSpell } from "../../backend/items/transmute/TransmutationSpell";
import { Spellcrafts } from "../../backend/spellcraft/Spellcrafts";
import { Spells } from "../../backend/spells/Spells";
import { t } from "../../utils/I18n";
import { SpellcraftInfo } from "../spellcraft/SpellcraftDetails";
import { useGameState } from "../utility/useGameState";
import { TransmuteSpellInfo } from "./CraftingDetails";
import { ItemInfo } from "./ItemInfo";
import { ItemTile } from "./ItemTile";

export interface ItemGridElement {
  itemOrSpellId: string;
  amount: number;
  params: any;
  disabled?: boolean;
  disableTooltip?: boolean;
  itemInfo?: ItemCompendiumInfo;
}

export enum ItemGridElementUsage {
  INVENTORY = "INVENTORY",
  TRANSMUTE = "TRANSMUTE",
  COMPENDIUM = "COMPENDIUM",
  SPELLCRAFT = "SPELLCRAFT",
}

export interface ItemGridProps {
  items: ItemGridElement[];
  location?: ItemGridElementUsage;
  onPressItem?: (itemGridElement: ItemGridElement) => void;
  onDoublePressItem?: (itemGridElement: ItemGridElement) => void;
  overrideMaxHeight?: number;
}

function ItemGridTooltipUnmemoized(props: {
  item: ItemGridElement | undefined;
  location: ItemGridElementUsage | undefined;
}) {
  if (props.item == null || props.location == ItemGridElementUsage.COMPENDIUM) {
    return null;
  }
  const itemGridElement = props.item;
  return useMemo(() => {
    if (props.location == ItemGridElementUsage.TRANSMUTE) {
      const spell = Spells.getById(
        itemGridElement.itemOrSpellId,
      ) as TransmutationSpell;
      return <TransmuteSpellInfo spell={spell} />;
    } else if (props.location == ItemGridElementUsage.SPELLCRAFT) {
      return (
        <SpellcraftInfo
          showTile={false}
          spellcraftId={Spellcrafts.getAll()
            .find(
              (spellcraft) =>
                spellcraft.getBaseItem().getId() ==
                itemGridElement.itemOrSpellId,
            )
            .getId()}
        />
      );
    }
    const item = Items.getById(itemGridElement.itemOrSpellId);
    return (
      <ItemInfo
        itemStack={{
          itemOccurrence: {
            itemId: item.getId(),
            params: itemGridElement.params,
          },
          amount: itemGridElement.amount,
        }}
        showTile={false}
      />
    );
  }, [JSON.stringify(itemGridElement), props.location]);
}

const ItemGridTooltip = React.memo(
  ItemGridTooltipUnmemoized,
  (prevProps, nextProps) =>
    JSON.stringify(prevProps) == JSON.stringify(nextProps),
);

function ItemGridElementUnmemoized(props: {
  itemGridElement: ItemGridElement;
  location: ItemGridElementUsage;
  index: number;
  onPress: (item: ItemGridElement) => void;
  onDoublePress: (item: ItemGridElement) => void;
}) {
  const { itemGridElement, index, onPress, onDoublePress } = props;
  const item =
    props.location == ItemGridElementUsage.TRANSMUTE
      ? (
          Spells.getById(itemGridElement.itemOrSpellId) as TransmutationSpell
        ).getItem()
      : Items.getById(itemGridElement.itemOrSpellId);
  const accessibilityMarker = useMemo(() => {
    let accessibilityMarker: React.ReactNode = null;
    if (index % 10 == 0) {
      accessibilityMarker = (
        <View
          style={styles.accessibilityHidden}
          accessible={true}
          accessibilityLabel={t("Item list position {{index}}", {
            index: index + 1,
          })}
          accessibilityRole="header"
          // @ts-ignore This prop is supported
          accessibilityLevel={5}
          accessibilityElementsHidden={false}
        />
      );
    }
    return accessibilityMarker;
  }, [index]);
  const tooltip = useMemo(
    () =>
      itemGridElement.disableTooltip || Platform.OS != "web" ? null : (
        <ItemGridTooltip item={itemGridElement} location={props.location} />
      ),
    [itemGridElement, props.location],
  );
  return useMemo(
    () => (
      <View>
        {accessibilityMarker}
        <ItemTile
          title={item.getName(itemGridElement.params)}
          onPress={onPress ? onPress.bind(null, itemGridElement) : undefined}
          onDoublePress={
            onDoublePress
              ? onDoublePress.bind(null, itemGridElement)
              : undefined
          }
          icon={item.getId()}
          quality={itemGridElement?.params?.itemQuality}
          disabled={itemGridElement.disabled}
          amount={itemGridElement.amount}
          tooltipIdSuffix={props.location}
          tooltip={tooltip}
        />
      </View>
    ),
    [
      accessibilityMarker,
      itemGridElement,
      tooltip,
      onPress,
      onDoublePress,
      props.location,
    ],
  );
}

const ItemGridTile = React.memo(ItemGridElementUnmemoized);

export function ItemGrid(props: ItemGridProps) {
  const theme = useTheme();
  const state = useGameState();
  const { height, width } = useWindowDimensions();
  const estimatedMaxHeight = Math.max(220, height - 80 - 300 - 300);
  let stateRef = useRef(state);
  stateRef.current = state;
  const topContainerStyle = useMemo(
    () => [styles.container, { borderColor: theme.colors.outlineVariant }],
    [theme],
  );
  const scrollViewStyle = useMemo(
    () => [
      props.overrideMaxHeight != null
        ? {
            maxHeight: props.overrideMaxHeight,
          }
        : { maxHeight: estimatedMaxHeight },
    ],
    [props.overrideMaxHeight, estimatedMaxHeight],
  );
  const insideContainerStyle = useMemo(
    () => [styles.gridContainer, { backgroundColor: theme.colors.background }],
    [theme],
  );
  return useMemo(() => {
    return (
      <View style={topContainerStyle}>
        <ScrollView style={scrollViewStyle} nestedScrollEnabled={true}>
          <View style={insideContainerStyle}>
            {props.items.map((itemGridElement, index) => (
              <ItemGridTile
                key={
                  itemGridElement.itemOrSpellId +
                  JSON.stringify(itemGridElement.params)
                }
                itemGridElement={itemGridElement}
                index={index}
                onPress={props.onPressItem}
                onDoublePress={props.onDoublePressItem}
                location={props.location}
              />
            ))}
          </View>
        </ScrollView>
      </View>
    );
  }, [
    JSON.stringify(props.items),
    props.onPressItem,
    props.onDoublePressItem,
    props.location,
    topContainerStyle,
    scrollViewStyle,
    theme,
  ]);
}

const styles = StyleSheet.create({
  container: {
    borderWidth: 2,
  },
  scrollView: {
    maxHeight: 220,
  },
  gridContainer: {
    flexDirection: "row",
    flexWrap: "wrap",
    justifyContent: "center",
    flex: 1,
  },
  accessibilityHidden: {
    width: 1,
    height: 1,
    position: "absolute",
    left: 0,
    top: 0,
  },
});
