import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import EtherealEelExtract from "../../../items/quest/EtherealEelExtract";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { HasteTemporaryEffect } from "../../../spells/air/Haste";
import { SpellElement } from "../../../spells/Elements";
import { Chilled } from "../../../temporaryeffects/Chilled";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { Wet } from "../../../temporaryeffects/Wet";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import {
  aoeEnemyAttackEffect,
  calculateAttackTarget,
  getCurrentEnemyHPFraction,
  getEnemyTurnCounter,
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { pushCombatActionResult } from "../../ExplorationActionResult";
import { hasCurrentActiveCompanion } from "../../Summoning";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class EtherealEel extends Enemy {
  getId(): string {
    return "etherealEel";
  }

  getName(): string {
    return t("Ethereal Eel");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 150000,
      attack: 400,
      defense: 250,
      attackDelay: 1.5,
      accuracy: 160,
      dodge: 400,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./etherealEel.png");
  }

  getLevel(): number {
    return 55;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 99999;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 9999;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (getEnemyTurnCounter(state) % 5 == 0 && Math.random() < 0.5) {
      return {
        name: t("Wide Tsunami"),
        delay: this.getAttackDelay(state) * 3,
        transform: (state) => {
          state = grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Enemy,
          })(state);
          state = grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Player,
          })(state);
          if (hasCurrentActiveCompanion(state)) {
            state = grantTemporaryEffect(Wet.getId(), 60, {
              target: AttackTarget.Familiar,
            })(state);
          }
          state = aoeEnemyAttackEffect(state, this.getAttack(state) * 3, {
            tags: [SpellElement.Water],
          });
          state = modifyEnemyCurrentHP(state, -300);
          state = pushCombatActionResult(state, {
            id: Math.random().toString(),
            damage: 300,
            isCrit: false,
            isHit: true,
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
            time: getSecondsPlayed(state),
          });
          return state;
        },
      };
    }
    if (
      !hasTemporaryEffect(state, Wet.getId(), AttackTarget.Enemy) &&
      !hasTemporaryEffect(state, Chilled.getId(), AttackTarget.Enemy)
    ) {
      return {
        name: t("Soak"),
        delay: this.getAttackDelay(state) * 0.2,
        transform: (state) =>
          grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Enemy,
          })(state),
      };
    }
    if (
      !hasTemporaryEffect(
        state,
        HasteTemporaryEffect.getId(),
        AttackTarget.Enemy,
      ) &&
      getCurrentEnemyHPFraction(state) < 0.5
    ) {
      return {
        name: t("Haste"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: grantTemporaryEffect(HasteTemporaryEffect.getId(), 30, {
          target: AttackTarget.Enemy,
          params: { commonBuff: { AttackDelayMultiplier: 0.75 } },
        }),
      };
    }
    if (Math.random() < 0.5) {
      return {
        name: t("Splash"),
        delay: this.getAttackDelay(state) * 1.1,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.1,
            {
              tags: [SpellElement.Water],
              cause: t("Splash"),
              onHit: (state, result) => {
                if (result.isHit) {
                  state = grantTemporaryEffect(Wet.getId(), 15, {
                    target: result.target,
                  })(state);
                }
                return state;
              },
            },
          );

          return state;
        },
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 24,
        chance: 1.0,
      },
      {
        itemId: EtherealEelExtract.getId(),
        amount: 1,
        chance: 1.0,
      },
    ];
  }

  isBoss(): boolean {
    return true;
  }

  getTips(): string {
    return t(
      "This eel is... it's almost as if it *wasn't there*! It is very hard to find, and it is mighty strong. **If we're not actively looking for it, we probably will not stand a chance!** That said, if we somehow ARE hunting for this enemy in particular, then we probably ARE strong enough; and in that case, this fight might not be THAT difficult... Just watch out for Wide Tsunami and avoid using Fire spells - and it'll be OK!",
    );
  }
}

export default new EtherealEel();
