import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Cherry } from "../../../items/equipment/pouch/Cherry";
import { standardEnemyAttackEffect } from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class BreezyRabbit extends Enemy {
  getId(): string {
    return "breezyRabbit";
  }

  getName(): string {
    return t("Breezy Rabbit");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 65,
      attack: 13,
      defense: 3,
      attackDelay: 1.0,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./breezyRabbit.png");
  }

  getLevel(): number {
    return 2;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 20;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 3;
  }

  getNextAction(state: GameState): BattlerAction {
    if (Math.random() < 0.1) {
      return {
        name: t("Jump adorably"),
        delay: this.getAttackDelay(state),
        transform: (state) => state,
      };
    }
    if (Math.random() < 0.2) {
      return {
        name: t("Double Attack"),
        delay: this.getAttackDelay(state) * 1.3,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.8);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.8);
          return state;
        },
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Cherry.getId(),
        amount: 1,
        chance: 0.05,
      },
    ];
  }
}

export default new BreezyRabbit();
