import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import { isSynchroUnlocked } from "../../backend/synchro/Synchro";
import { t } from "../../utils/I18n";
import { EquipmentSetButtons } from "../inventory/EquipmentSetButtons";
import { SynchroGridSelectors } from "../synchro/SynchroGridSelectors";
import { AutocastLoadoutLoadContainer } from "../utility/AutocastLoadoutLoadContainer";
import CategoryContainer from "../utility/CategoryContainer";
import { CheckBox } from "../utility/CheckBox";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";

export function StrategyContainer() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const theme = useTheme();

  const lastLoadoutUsed =
    state.global.autocast.savedStrategies?.[
      state.global.autocast.lastStrategyIndex
    ]?.name ?? null;

  const autoNextFloorCallback = useCallback(() => {
    stateRef.current.apply((state) => {
      state.run.autoAdvanceToNextFloor = !state.run.autoAdvanceToNextFloor;
      return state;
    });
  }, []);

  const autoBossCallback = useCallback(() => {
    stateRef.current.apply((state) => {
      state.run.autoAttemptBoss = !state.run.autoAttemptBoss;
      return state;
    });
  }, []);

  const fullyHealedCallback = useCallback(() => {
    stateRef.current.apply((state) => {
      state.options.waitUntilFullyHealedBeforeExplore =
        !state.options.waitUntilFullyHealedBeforeExplore;
      return state;
    });
  }, []);

  return useMemo(
    () => (
      <View style={styles.mainContainer}>
        <View style={styles.actionsContainer}>
          <CategoryContainer
            title={t("Equipment Sets")}
            containerStyle={styles.equipmentContainer}
            dividerColor={theme.colors.surfaceDisabled}
          >
            <View style={styles.categoryContents}>
              <EquipmentSetButtons />
            </View>
          </CategoryContainer>
          {isSynchroUnlocked(state) && (
            <CategoryContainer
              title={t("Synchro Grids")}
              containerStyle={styles.autocastContainer}
              dividerColor={theme.colors.surfaceDisabled}
            >
              <View style={styles.categoryContents}>
                <SynchroGridSelectors />
              </View>
            </CategoryContainer>
          )}
          <CategoryContainer
            title={t("Wizard Strategy")}
            containerStyle={styles.autocastContainer}
            dividerColor={theme.colors.surfaceDisabled}
          >
            <View style={styles.categoryContents}>
              <AutocastLoadoutLoadContainer />
              {lastLoadoutUsed != null && (
                <View style={styles.lastLoadedContainer}>
                  <Text>
                    {t("Last loaded: {{lastLoadoutUsed}}", { lastLoadoutUsed })}
                  </Text>
                </View>
              )}
            </View>
          </CategoryContainer>
          <CategoryContainer
            title={t("Exploration Config")}
            containerStyle={styles.autocastContainer}
            dividerColor={theme.colors.surfaceDisabled}
          >
            <View style={styles.categoryContents}>
              <View>
                <CheckBox
                  title={t("Automatically advance to the next floor")}
                  checked={state.run.autoAdvanceToNextFloor}
                  onPress={autoNextFloorCallback}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
              <View>
                <CheckBox
                  title={t("Automatically attempt fighting the boss")}
                  checked={state.run.autoAttemptBoss}
                  onPress={autoBossCallback}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
              <View>
                <CheckBox
                  title={t("Wait until fully healed before exploring")}
                  checked={state.options.waitUntilFullyHealedBeforeExplore}
                  onPress={fullyHealedCallback}
                  containerStyle={styles.checkboxContainer}
                />
              </View>
            </View>
          </CategoryContainer>
        </View>
      </View>
    ),
    [
      lastLoadoutUsed,
      state.run.autoAdvanceToNextFloor,
      state.run.autoAttemptBoss,
      state.options.waitUntilFullyHealedBeforeExplore,
      isSynchroUnlocked(state),
      theme,
    ],
  );
}

const styles = StyleSheet.create({
  mainContainer: {
    height: "100%",
  },
  actionsContainer: {
    flex: 1.5,
    marginRight: 8,
  },
  equipmentContainer: {
    paddingTop: 0,
  },
  autocastContainer: {
    paddingTop: 0,
    marginTop: 16,
  },
  categoryContents: {
    marginTop: 8,
  },
  lastLoadedContainer: {
    marginTop: 8,
  },
  checkboxContainer: {
    marginLeft: 0,
    marginTop: 8,
    padding: 0,
  },
});
