import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { registerTimeTickListener } from "../../timetick/TimeTick";
import { getSecondsPlayed } from "../../timetick/TotalTimePlayed";
import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { triggerEvent } from "../Events";
import { GameEvent } from "../GameEvent";

export type RandomEvent = {
  event: GameEvent;
  weight: number;
  eligible?: (state: GameState) => boolean;
  getParams?: (state: GameState) => any;
};

const randomEvents: Record<string, RandomEvent> = {};

export function registerRandomEventTrigger(
  event: GameEvent,
  weight: number,
  eligible?: (state: GameState) => boolean,
  getParams?: (state: GameState) => any,
): void {
  randomEvents[event.getId()] = {
    event,
    weight,
    eligible,
    getParams,
  };
}

const PITY_COUNTER_THRESHOLD = 60 * 60 * 3; // 3 hours
const PITY_COUNTER_SECOND_THRESHOLD = 60 * 60 * 6; // 6 hours
const PITY_COUNTER_VARIANCE = 0.3;
const PITY_MULTIPLIER = 40;
const PITY_MAX_PER_RUN = 2;

function registerPity(state: GameState): GameState {
  state.run.pityCounter += 1;
  const nextPitySec =
    PITY_COUNTER_SECOND_THRESHOLD +
    (Math.random() - 0.5) *
      (PITY_COUNTER_SECOND_THRESHOLD * PITY_COUNTER_VARIANCE);
  state.run.pityEnabledAtSeconds = getSecondsPlayed(state) + nextPitySec;
  return state;
}

export function isInPity(state: GameState): boolean {
  return (
    state.run.pityCounter < PITY_MAX_PER_RUN &&
    getSecondsPlayed(state) > (state.run.pityEnabledAtSeconds ?? Infinity)
  );
}

const randomEventWeightTags = [TransformationTags.RandomEventWeight];

export function triggerRandomEvent(state: GameState): GameState {
  // Initialize the pity counter, if needed
  if (state.run.pityEnabledAtSeconds === undefined) {
    const nextPitySec =
      PITY_COUNTER_THRESHOLD +
      (Math.random() - 0.5) * (PITY_COUNTER_THRESHOLD * PITY_COUNTER_VARIANCE);
    state.run.pityEnabledAtSeconds = getSecondsPlayed(state) + nextPitySec;
  }
  const eventsThatApply = Object.values(randomEvents).filter(
    (randomEvent) => !randomEvent.eligible || randomEvent.eligible(state),
  );
  if (eventsThatApply.length === 0) {
    return state;
  }
  const totalWeight = eventsThatApply.reduce((sumToNow, randomEvent) => {
    const weight = applyTransformations(
      randomEventWeightTags,
      state,
      randomEvent.weight,
      { randomEvent: randomEvent },
    );
    return sumToNow + weight;
  }, 0);
  let eventRoll = Math.random() * totalWeight;
  for (let idx in eventsThatApply) {
    const event = eventsThatApply[idx];
    const weight = applyTransformations(
      randomEventWeightTags,
      state,
      event.weight,
      { randomEvent: event },
    );
    eventRoll -= weight;
    if (eventRoll < 0) {
      const params = event.getParams ? event.getParams(state) : {};
      if (event.event.getStorylines().length > 0) {
        state = registerPity(state);
      }
      return triggerEvent(event.event, params)(state);
    }
  }
  return state;
}

export function debugRandomEvents(state: GameState): Record<string, number> {
  const eventsThatApply = Object.values(randomEvents).filter(
    (randomEvent) => !randomEvent.eligible || randomEvent.eligible(state),
  );
  const totalWeight = eventsThatApply.reduce((sumToNow, randomEvent) => {
    const weight = applyTransformations(
      randomEventWeightTags,
      state,
      randomEvent.weight,
      { randomEvent: randomEvent },
    );
    return sumToNow + weight;
  }, 0);
  const chancesPerEvent: Record<string, number> = {};
  eventsThatApply.forEach((event) => {
    const weight = applyTransformations(
      randomEventWeightTags,
      state,
      event.weight,
      { randomEvent: event },
    );
    chancesPerEvent[event.event.getId()] = weight / totalWeight;
  });
  return chancesPerEvent;
}

export function loadRandomEventTriggers(): void {
  registerTransformation(
    [[TransformationTags.RandomEventWeight]],
    "PityEventChance",
    t("Storyline Pity"),
    TransformationType.Multiplier,
    (state, params) => {
      const randomEvent: RandomEvent = params.randomEvent;
      if (randomEvent.event.getStorylines().length > 0 && isInPity(state)) {
        return PITY_MULTIPLIER;
      }
      return 1.0;
    },
  );
  registerTimeTickListener(
    "RandomEventTriggers",
    (state: GameState, elapsedSec: number) => {
      const secsPlayed = getSecondsPlayed(state) + state.run.extraEventSecs;
      if (secsPlayed > (state.run.nextRandomEvent || 0)) {
        state = triggerRandomEvent(state);

        // The next random event will occur in 8 to 12 minutes. However,
        // this period becomes longer and longer as more seconds have been played
        // in the current run: 40 to 60 mins after 24 hours, for example.
        // This rewards quickly triggering random events after retirement and
        // prevents low quality random event spam later in the game.
        // This is capped at 24 in-game hours.
        const periodMultiplier = Math.max(
          1.0 + Math.min(secsPlayed, 60 * 60 * 24) / (60 * 60 * 6),
          1.0,
        );
        state.run.nextRandomEvent =
          (8 + Math.random() * 4) * 60 * periodMultiplier + secsPlayed;
      }
      return state;
    },
  );
}
