import { formatNumber } from "../../../../../utils/FormattingUtils";
import { t } from "../../../../../utils/I18n";
import { ActionEffect } from "../../../../action/Action";
import { AttackTarget } from "../../../../exploration/AttackTarget";
import { CombatActionResultListeners } from "../../../../exploration/ExplorationActionResult";
import { GameState } from "../../../../GameState";
import { Resource } from "../../../../Resources";
import { SpellElement } from "../../../../spells/Elements";
import { poison } from "../../../../temporaryeffects/Poison";
import { grantTemporaryEffect } from "../../../../temporaryeffects/TemporaryEffects";
import { TransformationTags } from "../../../../transformation/TransformationTags";
import { EquipmentSlot, getEquippedItem } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class Dagger extends EquippableItem {
  getId(): string {
    return "dagger";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Hand;
  }
  getPicture() {
    return require("./dagger.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Dagger");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t("A small blade with the power of Poison.");
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 800;
  }

  poisonTags = [TransformationTags.TemporaryEffectMagnitude, poison.getId()];
  poisonDurationTags = [
    TransformationTags.TemporaryEffectDuration,
    poison.getId(),
  ];
  protected getBaseExtraItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    return {
      poisonStrength: {
        value: 8,
        tags: this.poisonTags,
        unit: ":hp:/sec",
      },
      poisonDuration: {
        value: 1,
        tags: this.poisonDurationTags,
        unit: " sec",
      },
    };
  }

  getPoisonStrength(state: GameState, params: ItemParams): number {
    return this.getItemEffects(state, params, "poisonStrength").poisonStrength;
  }

  getEffectExtra(state: GameState, params: ItemParams): string {
    const poisonStrength = this.getPoisonStrength(state, params);
    const poisonDuration = this.getItemEffects(
      state,
      params,
      "poisonDuration",
    ).poisonDuration;
    return t(
      "On hit, applies ^-{{poisonStrength}}^<{{strengthExplanation}}>:hp:/sec Poison for ^{{poisonDuration}}^<{{durationExplanation}}> sec",
      {
        poisonStrength: formatNumber(poisonStrength),
        strengthExplanation: this.getItemEffectExplanations(
          state,
          params,
          "poisonStrength",
        ).poisonStrength,
        poisonDuration: formatNumber(poisonDuration, { showDecimals: true }),
        durationExplanation: this.getItemEffectExplanations(
          state,
          params,
          "poisonDuration",
        ).poisonDuration,
      },
    );
  }

  getAttackBonusBase(params: ItemParams): number {
    return 20;
  }

  getAttackDelayBonusRatioBase(params: ItemParams): number {
    return -0.3;
  }
}

export const dagger = new Dagger();

CombatActionResultListeners.register((state, result) => {
  if (dagger.isEquippedWithAnyParams(state)) {
    if (
      result.source == AttackTarget.Player &&
      result.tags.includes(TransformationTags.AutoAttack) &&
      result.isHit
    ) {
      const effects = dagger.getItemEffects(
        state,
        getEquippedItem(state, EquipmentSlot.Hand).params,
      );
      state = grantTemporaryEffect(poison.getId(), effects.poisonDuration, {
        mode: "add",
        target: result.target,
        params: {
          magnitude: dagger.getPoisonStrength(
            state,
            getEquippedItem(state, EquipmentSlot.Hand).params,
          ),
        },
      })(state);
    }
  }
  return state;
});

class TransmuteDagger extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return dagger;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Poison: 5,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 200,
        PoisonEssence: 2500,
      },
      items: {
        elementalShardAir: 1,
        elementalShardPoison: 4,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Poison;
  }
}

export const transmuteDagger = new TransmuteDagger();
