import { t } from "../../../../utils/I18n";
import { Achievement } from "../../../Achievement";
import { registerAchievementUnlockerFromStoryline } from "../../../Achievements";
import { monstiumCatalysts } from "../../../enhancement/other/MonstiumCatalysts";
import { Flag } from "../../../Flag";
import { setFlag, SetFlagListeners } from "../../../Flags";
import { clearResourceCapCache, Resource } from "../../../Resources";
import {
  registerPostRetirementListener,
  registerRetirementListener,
} from "../../../Retire";
import {
  PARTIALLY_UNLOCKED_CHANNELING_MULTIPLIER,
  PARTIALLY_UNLOCKED_STORAGE_MULTIPLIER,
} from "../../../spells/ElementMultipliers";
import {
  getElementLevel,
  getFullyUnlockedElements,
  getTotalExpRequiredForLevel,
  grantElementExp,
  hasElementBeenPartiallyUnlockedInPreviousRun,
  SpellElement,
  unlockElement,
} from "../../../spells/Elements";
import { Storylines } from "../../../storylines/Storylines";
import { clearResearcherExpCache } from "../../../study/Study";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { TomeOfToxicKnowledge } from "./TomeOfToxicKnowledgeStoryline";

export function loadTomeOfToxicKnowledge() {
  Storylines.register(TomeOfToxicKnowledge);
  const poisonUnlockEventBuilder = buildEvent(
    "tomeOfToxicKnowledge",
    t("(Storyline) Tome of Toxic Knowledge"),
    [EventTag.STORYLINE],
  ).setStorylineIds(TomeOfToxicKnowledge.getId());

  poisonUnlockEventBuilder.message(
    t(`On the desk of the Mad Alchemist, you notice a book with a purple cover. It immediately catches your attention, as it says: *"Tome of Toxic Knowledge"* - and it depicts *a Man-Eating Plant* on the cover. You are thinking this Tome might be just what you are looking for - you have been having such a hard time with Poison all this time!
    
The Mad Alchemist is facing away. You look at your hired wizard, who seems to understand what you're about to do and quietly steps away. Then you tiptoe and pick up the book, store it, and head out of the room... and then slightly after, out of sight, you *run*, to make sure he cannot catch you!`),
  );
  poisonUnlockEventBuilder
    .message(
      t(`Back at home, your hired wizard looks at it with you. She exclaims: "It is so detailed - it is like a basic but thorough explanation of Poison as an element!"

As you skim it with her, you realize she speaks the truth. You read an excerpt from the introduction: *"The Poison element is essential not only for the creation of potent substances that can weaken, but also for a wide variety of magical substances. Proper management of Poison is also critical for obtaining, storing, and working with, the important wizardry resource, Monstium."* 
      
You also notice there is not only basic knowledge about the element written down here, but also many details about advanced applications - things you would have never been able to even dream of! Even now, you still can't understand most of it, but this Tome feels like a very valuable reference material.`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = TomeOfToxicKnowledge.complete(state);
          if (
            hasElementBeenPartiallyUnlockedInPreviousRun(
              state,
              SpellElement.Poison,
            )
          ) {
            state = TomeOfToxicKnowledge.activateBonus(state);
            const poisonLevel = getElementLevel(state, SpellElement.Poison);
            state = unlockElement(SpellElement.Poison)(state);
            state.run.elementExperience[SpellElement.Poison] = 0;
            delete state.run.elementExponents[SpellElement.Poison];
            state = grantElementExp(
              SpellElement.Poison,
              getTotalExpRequiredForLevel(
                state,
                poisonLevel,
                SpellElement.Poison,
              ),
            )(state);
          }
          state = clearResourceCapCache(state);
          state = clearResearcherExpCache(state);
          return state;
        },
      },
      "end",
    );
  poisonUnlockEventBuilder
    .message(
      t(`You take the Tome and store it in a special bookshelf. You are saving this bookshelf only for those volumes that are *particularly* useful. And this one certainly is. Although, you hope you will be able to have the time to read it...

/lastLine/`),
    )
    .tag("end")
    .end();

  const poisonUnlockEvent = poisonUnlockEventBuilder.build();

  SetFlagListeners.register((state, flag) => {
    if (!state.run.primaryElement) {
      return state;
    }
    if (
      flag == Flag.VastDesertUnlocked &&
      !poisonUnlockEvent.hasTriggered(state) &&
      !TomeOfToxicKnowledge.isCompleted(state)
    ) {
      return triggerEvent(poisonUnlockEvent, {
        lastLine: hasElementBeenPartiallyUnlockedInPreviousRun(
          state,
          SpellElement.Poison,
        )
          ? t(
              '**You have completed the "Tome of Toxic Knowledge" Storyline and have fully unlocked the Poison element! There will be no more penalties to Poison or :poisonessence:. Also, in future retirements, Poison will be available from the start, with no penalties, and it will be possible to select it as a primary element! And as a bonus, you will begin new runs with Monstruariums and Monstium Catalysts already unlocked!**',
            )
          : t(
              '**You have completed the "Tome of Toxic Knowledge" Storyline! There will be no more penalties to :poisonessence:. Also, in future retirements, Poison will be fully unlocked! This means that it will be available from the start, with no penalties, and it will be possible to select it as a primary element! And as a bonus, you will begin new runs with Monstruariums and Monstium Catalysts already unlocked!**',
            ),
      })(state);
    }
    return state;
  }, poisonUnlockEvent.getId());

  registerRetirementListener(
    "poisonUnlockRetirementListener",
    (state, isMock) => {
      if (
        TomeOfToxicKnowledge.isCompleted(state) &&
        !getFullyUnlockedElements(state).includes(SpellElement.Poison)
      ) {
        state = unlockElement(SpellElement.Poison)(state);
      }
      return state;
    },
  );

  registerPostRetirementListener(
    "poisonUnlockPostRetirementListener",
    (state) => {
      if (getFullyUnlockedElements(state).includes(SpellElement.Poison)) {
        state = setFlag(Flag.MonstruariumsUnlocked)(state);
        state = monstiumCatalysts.setPurchased(state);
      }
      return state;
    },
  );

  registerTransformation(
    [[Resource.PoisonEssence, TransformationTags.Cap]],
    "afterPoisonUnlockCap",
    TomeOfToxicKnowledge.getName(),
    TransformationType.Multiplier,
    (state) =>
      TomeOfToxicKnowledge.isCompleted(state) &&
      !getFullyUnlockedElements(state).includes(SpellElement.Poison)
        ? 1 / PARTIALLY_UNLOCKED_STORAGE_MULTIPLIER
        : 1.0,
  );

  registerTransformation(
    [
      [SpellElement.Poison, TransformationTags.ChannelingEfficiency],
      [SpellElement.Poison, TransformationTags.ExpGain],
    ],
    "afterPoisonUnlockChanneling",
    TomeOfToxicKnowledge.getName(),
    TransformationType.Multiplier,
    (state) =>
      TomeOfToxicKnowledge.isCompleted(state) &&
      !getFullyUnlockedElements(state).includes(SpellElement.Poison)
        ? 1 / PARTIALLY_UNLOCKED_CHANNELING_MULTIPLIER
        : 1.0,
  );

  registerAchievementUnlockerFromStoryline(
    TomeOfToxicKnowledge,
    Achievement.TOME_OF_TOXIC_KNOWLEDGE,
  );
}
