import { StyleSheet, View } from "react-native";
import { ScrollView } from "react-native-gesture-handler";
import { useTheme } from "react-native-paper";
import { DungeonFloor } from "../../backend/exploration/dungeons/DungeonFloor";
import {
  Dungeons,
  getUnlockedDungeons,
} from "../../backend/exploration/dungeons/Dungeons";
import { getFloorsAvailableForFamiliars } from "../../backend/familiars/Familiar";
import { keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { Button } from "./Button";
import CategoryContainer from "./CategoryContainer";
import Styles from "./Styles";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

export function DungeonFloorSelect(props: {
  familiarId: string | undefined;
  onSelect: (dungeonFloorId: string) => void;
}) {
  const state = useGameState();

  const dungeons = getUnlockedDungeons(state);
  const dungeonFloors = getFloorsAvailableForFamiliars(state);
  const floorsByDungeon: Record<string, DungeonFloor[]> = {};
  for (let dungeon of dungeons) {
    const floors = dungeon.getDungeonFloors();
    const filteredFloors = floors.filter((floor) =>
      dungeonFloors.includes(floor),
    );
    if (filteredFloors.length > 0) {
      floorsByDungeon[dungeon.getId()] = filteredFloors;
    }
  }
  const theme = useTheme();

  const floorsView = (
    <View>
      {keysOf(floorsByDungeon).map((dungeonId) => {
        const dungeon = Dungeons.getById(dungeonId);
        return (
          <CategoryContainer title={dungeon.getName()} key={dungeon.getId()}>
            <View style={Styles.mt8}>
              {floorsByDungeon[dungeonId].map((floor) => {
                return (
                  <View
                    style={[Styles.flexRowSpaceBetween, Styles.mv4]}
                    key={floor.getId()}
                  >
                    <View>
                      <Text>{floor.getFloorName()}</Text>
                      {props.familiarId != null &&
                        floor.isDangerousForFamiliar(
                          state,
                          props.familiarId,
                        ) && (
                          <Text style={{ color: theme.colors.error }}>
                            {t("Caution - dangerous!")}
                          </Text>
                        )}
                    </View>
                    <Button
                      style={Styles.ml16}
                      onPress={() => props.onSelect(floor.getId())}
                    >
                      {t("Explore")}
                    </Button>
                  </View>
                );
              })}
            </View>
          </CategoryContainer>
        );
      })}
    </View>
  );

  return (
    <View style={[Styles.flex, styles.bigContainer]}>
      <ScrollView style={[{ flexGrow: 0 }, Styles.mt8]}>
        {floorsView}
      </ScrollView>
    </View>
  );
}

const styles = StyleSheet.create({
  spellButtonTitleContainer: {
    flex: 1,
    flexDirection: "row",
    alignItems: "center",
  },
  spellButtonTitleOnlyContainer: {
    flex: 1,
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
  },
  spellButtonCostContainer: {
    flexDirection: "row",
    width: 100,
    justifyContent: "flex-end",
  },
  spellManaIcon: {
    height: 16,
    width: 16,
    justifyContent: "flex-end",
    alignItems: "center",
    marginLeft: 2,
  },
  bigContainer: {
    width: 340,
  },
});
