import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { clearResearcherExpCache } from "../../study/Study";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";
import { understandingOfMagic } from "./UnderstandingOfMagic";

class DeeperUnderstandingOfMagic extends Enhancement {
  getId(): string {
    return "deeperUnderstandingOfMagic";
  }
  getBaseName(): string {
    return t("Deeper Understanding of Magic");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_OTHER;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 3000,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 8,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [understandingOfMagic];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Study how magic works even deeper, to be able to discover new applications more efficiently.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("+60% Element exp earned from Studying");
  }
}

export const deeperUnderstandingOfMagic = new DeeperUnderstandingOfMagic();

registerTransformation(
  [[TransformationTags.ExpGain, TransformationTags.Study]],
  deeperUnderstandingOfMagic.getId(),
  deeperUnderstandingOfMagic.getBaseName(),
  TransformationType.Multiplier,
  (state) => (deeperUnderstandingOfMagic.isPurchased(state) ? 1.6 : 1.0),
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == deeperUnderstandingOfMagic) {
    state = clearResearcherExpCache(state);
  }
  return state;
}, deeperUnderstandingOfMagic.getId());
