import { MaterialIcons } from "@expo/vector-icons";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import {
  ScrollView,
  StyleSheet,
  View,
  useWindowDimensions,
} from "react-native";
import { Modal, useTheme } from "react-native-paper";
import { GameState } from "../../backend/GameState";
import {
  addGameStateListener,
  clearGameStateListener,
} from "../../backend/GameStateListeners";
import { getUICommands } from "../../backend/UICommands";
import {
  EventOccurrenceData,
  executeEventOccurrenceAction,
  getEventMessageById,
  getEventOccurrenceById,
  removeOccurrenceFromQueue,
} from "../../backend/events/Events";
import { EventMessage } from "../../backend/events/GameEvent";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import { IconButton } from "../utility/IconButton";
import { Markdown } from "../utility/Markdown";
import { disabledColorByTheme } from "../utility/Miscellaneous";
import { Text } from "../utility/Text";
import { UIContext, useUIStateFromContext } from "../utility/UIContext";
import { useGameState } from "../utility/useGameState";

export function EventOverlay() {
  const state = useGameState();
  const uiContext = useContext(UIContext);
  const isPickerOpen = uiContext.getUIState("picker_open");
  const isEndingOpen = uiContext
    .getUIState("active_sections")
    ?.includes("Ending");
  const [currentEventOccurrenceId, setCurrentEventOccurrenceId] =
    useUIStateFromContext(
      uiContext,
      "current_event_occurrence_id",
      undefined as string | undefined,
    );

  const processUICommand = useCallback((uiCommand: string) => {
    if (uiCommand == "closeEventOverlay") {
      setCurrentEventOccurrenceId(undefined);
      setCurrentLookbackMessageId("");
    }
  }, []);

  const processUICommands = useCallback((state: GameState) => {
    const uiCommands = getUICommands(state);
    uiCommands.forEach(processUICommand);
    state.run.uiCommands = uiCommands.filter(
      (uiCommand) => uiCommand != "closeEventOverlay",
    );
    return state;
  }, []);

  useEffect(() => {
    addGameStateListener("eventOverlayUiCommand", (newState) => {
      const uiCommands = getUICommands(newState);
      if (
        uiCommands.filter((command) => command === "closeEventOverlay").length >
        0
      ) {
        return (state) => processUICommands(state);
      }
    });
    return () => clearGameStateListener("eventOverlayUiCommand");
  }, []);

  const currentEventOccurrenceIdRef = useRef(currentEventOccurrenceId);
  currentEventOccurrenceIdRef.current = currentEventOccurrenceId;

  const [currentLookbackMessageId, setCurrentLookbackMessageId] = useState("");

  useEffect(() => {
    addGameStateListener("event_overlay", (newState) => {
      if (isPickerOpen || isEndingOpen) {
        return;
      }
      if (
        newState.run.eventQueue.length > 0 &&
        (!currentEventOccurrenceIdRef.current ||
          getEventOccurrenceById(
            newState,
            currentEventOccurrenceIdRef.current,
          ) == null)
      ) {
        // Get a new event
        let newEventOccurrenceId: string | null | undefined = null;
        const eventsToRemoveFromQueue: string[] = [];

        do {
          newEventOccurrenceId = newState.run.eventQueue.pop();
          if (newEventOccurrenceId != null) {
            eventsToRemoveFromQueue.push(newEventOccurrenceId);
            const occurrence = getEventOccurrenceById(
              newState,
              newEventOccurrenceId,
            );
            const message = occurrence?.currentMessageId;
            if (occurrence != null && message != null) {
              if (
                (occurrence?.timestampSecondsPlayed ?? 0) <= 0.1 &&
                getEventMessageById(message)?.isTerminal()
              ) {
                newEventOccurrenceId = null;
              }
            }
          }
        } while (
          newState.run.eventQueue.length > 0 &&
          newEventOccurrenceId == null
        );

        if (newEventOccurrenceId) {
          setCurrentEventOccurrenceId(newEventOccurrenceId);
          setCurrentLookbackMessageId("");

          // This next line is required because otherwise, if many events are
          // in the queue before the next rerender, we will swallow them
          currentEventOccurrenceIdRef.current = newEventOccurrenceId;
        }

        return (state) =>
          eventsToRemoveFromQueue.reduce(
            (accumulatedState, eventToRemove) =>
              removeOccurrenceFromQueue(eventToRemove)(accumulatedState),
            state,
          );
      }
    });
    return () => clearGameStateListener("event_overlay");
  }, [isPickerOpen, isEndingOpen]);

  let eventOccurrence: EventOccurrenceData | null | undefined = null,
    eventMessage: EventMessage | null | undefined = null,
    optionDescriptions: string[] | null = null,
    totalText: string | null = null,
    messageInLookbackIdx: number = -1,
    messageLength: number = 0,
    totalMessageCount: number = -1;
  if (currentEventOccurrenceId != null) {
    eventOccurrence = getEventOccurrenceById(state, currentEventOccurrenceId);
    messageInLookbackIdx =
      eventOccurrence?.messageHistory?.findIndex(
        (value) => value === currentLookbackMessageId,
      ) ?? -1;
    if (messageInLookbackIdx < 0) {
      eventMessage = (eventOccurrence?.currentMessageId &&
        getEventMessageById(eventOccurrence.currentMessageId)) as EventMessage;
    } else {
      eventMessage = getEventMessageById(
        currentLookbackMessageId as string,
      ) as EventMessage;
    }
    if (eventMessage) {
      optionDescriptions = eventMessage
        .getAllowedOptions(state, eventOccurrence?.params)
        .map((option) => option.description + option.isEnabled);
    }
    messageLength = eventOccurrence?.messageHistory?.length ?? 0;
    if (messageLength > 0) {
      totalMessageCount = messageLength + 1;
      const idxToDisplay =
        messageInLookbackIdx >= 0
          ? messageInLookbackIdx + 1
          : totalMessageCount;
      if (idxToDisplay < totalMessageCount) {
        totalText = `(${idxToDisplay}/${totalMessageCount})`;
      }
    }
  }

  const { height, width } = useWindowDimensions();

  const theme = useTheme();

  const disabledIconColor = disabledColorByTheme(theme);

  return useMemo(() => {
    if (currentEventOccurrenceId && eventMessage) {
      const disableBackArrow =
        messageInLookbackIdx === 0 || messageLength === 0;
      const disableForwardArrow =
        messageInLookbackIdx < 0 ||
        messageInLookbackIdx > totalMessageCount - 2;
      return (
        <Modal
          visible={true}
          dismissable={false}
          contentContainerStyle={[
            styles.overlay,
            {
              maxWidth: Math.min(width - 16, 640),
              maxHeight: height - 64,
              backgroundColor: theme.colors.background,
            },
          ]}
        >
          <View style={styles.overlayContainer}>
            <IconButton
              icon={() => (
                <MaterialIcons
                  name="close"
                  size={24}
                  color={theme.colors.onPrimary}
                />
              )}
              accessibilityLabel="Close"
              style={styles.overlayCloseButton}
              mode="contained"
              containerColor={theme.colors.primary}
              onPress={() => {
                setCurrentEventOccurrenceId(undefined);
                setCurrentLookbackMessageId("");
              }}
            />

            <View style={styles.titleContainer}>
              <View
                style={[
                  styles.titleTextContainer,
                  { maxWidth: messageLength > 0 ? width - 164 : width - 68 },
                ]}
              >
                <Text h1>
                  {eventMessage.getTitle(eventOccurrence?.params)} {totalText}{" "}
                </Text>
              </View>
              {messageLength > 0 && (
                <View style={styles.historyContainer}>
                  <IconButton
                    disabled={disableBackArrow}
                    icon={() => (
                      <MaterialIcons
                        name="arrow-back"
                        size={24}
                        color={
                          disableBackArrow
                            ? theme.colors.onSurfaceDisabled
                            : theme.colors.onPrimary
                        }
                      />
                    )}
                    accessibilityLabel={t("Previous Message")}
                    style={styles.historyButton}
                    containerColor={
                      disableBackArrow
                        ? theme.colors.surfaceDisabled
                        : theme.colors.primary
                    }
                    onPress={() =>
                      setCurrentLookbackMessageId(
                        messageInLookbackIdx > 0
                          ? eventOccurrence?.messageHistory?.[
                              messageInLookbackIdx - 1
                            ] ?? ""
                          : eventOccurrence?.messageHistory?.[
                              totalMessageCount - 2
                            ] ?? "",
                      )
                    }
                  />
                  <IconButton
                    disabled={disableForwardArrow}
                    accessibilityLabel={t("Next Message")}
                    containerColor={
                      disableForwardArrow
                        ? theme.colors.surfaceDisabled
                        : theme.colors.primary
                    }
                    style={styles.historyButton}
                    icon={() => (
                      <MaterialIcons
                        name="arrow-forward"
                        size={24}
                        color={
                          disableForwardArrow
                            ? theme.colors.onSurfaceDisabled
                            : theme.colors.onPrimary
                        }
                      />
                    )}
                    onPress={() =>
                      setCurrentLookbackMessageId(
                        messageInLookbackIdx < totalMessageCount - 2
                          ? eventOccurrence?.messageHistory?.[
                              messageInLookbackIdx + 1
                            ] ?? ""
                          : "",
                      )
                    }
                  />
                </View>
              )}
            </View>
            <View style={{ maxHeight: height - 150 }}>
              <ScrollView style={styles.scrollView}>
                <View style={styles.description}>
                  <Markdown>
                    {eventMessage.getDescription(
                      state,
                      eventOccurrence?.params,
                    )}
                  </Markdown>
                </View>
                {messageInLookbackIdx < 0 && (
                  <View style={styles.optionRow}>
                    {eventMessage
                      .getAllowedOptions(state, eventOccurrence?.params)
                      .map((option) => {
                        const optionPress = () => {
                          if (!option.nextMessage && !option.transform) {
                            setCurrentEventOccurrenceId(undefined);
                            setCurrentLookbackMessageId("");
                            return;
                          }
                          state.apply(
                            executeEventOccurrenceAction(
                              currentEventOccurrenceId,
                              option,
                            ),
                          );
                        };
                        return (
                          <Button
                            key={option.description}
                            onPress={optionPress}
                            style={[
                              styles.optionButton,
                              { maxWidth: Math.min(width - 70, 586) },
                            ]}
                            TextComponent={View}
                            disabled={!option.isEnabled}
                          >
                            <Text
                              style={[
                                {
                                  color: theme.colors.onPrimary,
                                },
                                !option.isEnabled && {
                                  color: theme.colors.onSurfaceDisabled,
                                },
                              ]}
                            >
                              {option.description}
                            </Text>
                          </Button>
                        );
                      })}
                  </View>
                )}
              </ScrollView>
            </View>
          </View>
        </Modal>
      );
    }
    return null;
  }, [
    JSON.stringify(eventOccurrence),
    optionDescriptions?.join(","),
    messageInLookbackIdx,
    currentLookbackMessageId,
    width,
    height,
    theme,
    disabledIconColor,
  ]);
}

const styles = StyleSheet.create({
  overlay: {
    alignSelf: "center",
    marginHorizontal: 16,
    maxWidth: 640,
  },
  overlayContainer: {
    paddingVertical: 16,
    paddingHorizontal: 8,
  },
  historyButton: {
    height: 32,
    width: 32,
    borderRadius: 16,
    marginLeft: 8,
  },
  overlayCloseButton: {
    height: 32,
    width: 32,
    position: "absolute",
    right: -16,
    top: -16,
  },
  overlayCloseButtonInner: {
    paddingHorizontal: 0,
    paddingVertical: 0,
    height: 32,
  },
  titleContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  titleTextContainer: {},
  historyContainer: {
    marginLeft: 8,
    flexDirection: "row",
    width: 80,
    marginRight: 32,
  },
  description: {
    marginBottom: 8,
  },
  scrollView: {
    flexGrow: 0,
    marginTop: 8,
  },
  optionRow: {
    marginTop: 8,
    flexDirection: "row",
    flexWrap: "wrap",
    gap: 12,
    justifyContent: "space-around",
  },
  optionButton: {
    maxWidth: 600,
  },
});
