import { t } from "../../utils/I18n";
import { isAutocastEverUnlocked } from "../autocast/Autocast";
import { CombatClassesStoryline } from "../classes/CombatClassesStoryline";
import { isEnhancementEverUnlocked } from "../enhancement/Enhancements";
import { manaSteamers } from "../enhancement/other/ManaSteamers";
import { BookOfMonsters } from "../events/storyline/pretraining/BookOfMonstersStoryline";
import { permacastStoryline } from "../events/storyline/sewers/PermaCast";
import { isDungeonFloorEverCompleted } from "../exploration/dungeons/Dungeons";
import SewerEntrance from "../exploration/dungeons/sewers/SewerEntrance";
import { isExplorationEverUnlocked } from "../exploration/Exploration";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasWorldFlag } from "../Flags";
import {
  hasEverUnlockedCrafting,
  isInventoryEverUnlocked,
} from "../items/Items";
import { isRetirementUnlocked } from "../Retire";
import { isStudyEverUnlocked } from "../study/Study";
import { isSynchroUnlocked } from "../synchro/Synchro";
import { HelpCategory, Helps } from "./Helps";

export function loadHelpsDemo() {
  Helps.reset();

  // Introduction

  Helps.register({
    id: "welcome",
    category: HelpCategory.Introduction,
    title: t("Welcome to Magic Research 2!"),
    order: 0,
    description: t(`**Welcome to Magic Research 2!**
      
In Magic Research 2, you are a rookie wizard with a single ambition: to find or create the Philosopher's Stone, a legendary magical item that is said to be able to cure any illness. As you do so, you will learn more than you ever imagined about Magic and about the world around you.

In the top left corner, you will find **the menu button** which shows you a breakdown of your current resources and let you access various areas of the game.

In the top right corner, you will find **the notification bell button** while takes you to the list of events that have happened recently and allows you to see more information about them or interact with them.

The other important button is usually in the bottom right corner: **the magic wand button**. This will take you to the magic menu, where you can cast any spell you may have learned.

Depending on screen size, some of those buttons can be in different positions on the screen, or the menus they open will be visible at all times. If your screen is wide enough, the menu on the left may also have heart-shaped icons - these will allow you to open screens side-by-side.

**Note: at the bottom of the Home screen, there will be a "Current Goal" section. You can progress through the game by following what it says.**`),
  });

  // Screens Overview
  Helps.register({
    id: "homeScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Home"),
    order: 1000,
    description:
      t(`The :home:**Home** screen is where you can do activities related to your own home - your "base of operations" as a wizard.
    
In the Home screen, you will be able to manually gather :mana:Mana at a slow rate, as well as use it on Channeling spells to obtain Essences, the basic resources of the game.

Essences have many uses, but within the Home screen, you can utilize them to create *Furniture*. Furniture are objects that decorate or otherwise fill in your Home and have various effects.

Your Home has a limited amount of :land:Land available for Furniture. You can expand the amount of :land: available by purchasing more from the same screen, but it is expensive and requires :coins:Coins. The choice of what furniture you decide to have in your home and in what amounts is one of the biggest challenges of the game.`),
  });
  Helps.register({
    id: "studyScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Study"),
    order: 1010,
    isVisible(state) {
      return isStudyEverUnlocked(state);
    },
    description:
      t(`The :study:**Study** screen allows you to further your knowledge about the Elements, which is key to advancing in the game! Studying allows you to get Element exp over time, which eventually will let you level up your Elements to unlock new spells and get other bonuses.
      
In this screen, you are able to decide how to distribute your researchers between the Elements. You start with a single researcher (yourself) but you will find a way to get more later in the game. Note that your Primary Element will earn Element exp twice as fast.

If you decide to let the game auto-assign your researchers, there are a few options. "Spread evenly" will try to keep the same amount of researchers on every Element. "Most researchers" will assign all the researchers to the Element that already has the most researchers assigned to it. "Spread, then most researchers" will make sure there is at least one researcher assigned to every Element, then it will assign the rest to the Element that has the most Researchers. If more than one Element fits the definition, it will prioritize the Primary Element over non-Primary Elements.`),
  });
  Helps.register({
    id: "inventoryScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Inventory"),
    order: 1020,
    isVisible(state) {
      return isInventoryEverUnlocked(state);
    },
    description:
      t(`The :inventory:**Inventory** screen will show you a list of items, which you may have obtained in your journey via Transmutation or other means!

*There is no limit to the amount of items you may have.*

The screen is divided into two parts:

At the top is a list of equipped items, which can be individually unequipped.

At the bottom is the actual list of items shown as a grid. You may filter items by type - this feature may be useful later in the game. Pressing an item in the grid will show its details and often allow to use, equip, or sell the item to obtain :coins:.`),
  });
  Helps.register({
    id: "transmutationScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Transmutation"),
    order: 1030,
    isVisible(state) {
      return hasEverUnlockedCrafting(state);
    },
    description:
      t(`In the :transmute:**Transmute** screen, you will be able to Transmute items! Transmutation lets you transform items, elemental essences and other resources into new items.

The screen shows your possible Transmutations as a grid. You may select one item to see its details, cost, and Transmute it. You can unlock more Transmutations by raising your Element levels.

Once you have Transmuted an item, it will go straight to your :inventory:Inventory and you should be able to see it in that screen.`),
  });
  Helps.register({
    id: "wizardsScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Wizards"),
    order: 1040,
    isVisible(state) {
      return isAutocastEverUnlocked(state);
    },
    description:
      t(`The :wizards:**Wizards** screen lets you give instructions to your hired Wizards, who will cast spells as you ask - effectively letting you automate a large part of the game!
      
This screen, and its feature, is quite complex. You can learn more about this screen in other Help items in the Wizards category.`),
  });
  Helps.register({
    id: "explorationScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Exploration"),
    order: 1050,
    isVisible(state) {
      return isExplorationEverUnlocked(state);
    },
    description:
      t(`In the :exploration:**Exploration** screen, you can delve through various areas as you further your quest for the Philosopher's Stone!
      
This screen and feature is quite complex. You can learn more about this screen in other Help items in the Exploration category.`),
  });
  Helps.register({
    id: "enhancementScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Enhance"),
    order: 1060,
    isVisible(state) {
      return isEnhancementEverUnlocked(state);
    },
    description:
      t(`In the :enhance:**Enhance** screen, you can spend your hard-earned :monstium:Monstium, together with :paper:Paper, to obtain powerful semi-permanent bonuses and greatly improve your capabilities!
      
Enhancements will last for the rest of the run, but they do not persist through Retirement.

You can obtain :monstium:Monstium from defeating enemies in Exploration. On the other hand, :paper:Paper can be Transmuted, and you will need lots of it - if you are able to, consider automating their production with Wizards!`),
  });
  Helps.register({
    id: "synchroScreen",
    category: HelpCategory.ScreensOverview,
    title: t("Synchro"),
    order: 1065,
    isVisible(state) {
      return isSynchroUnlocked(state);
    },
    description:
      t(`In the :synchro:**Synchro** screen, you can arrange your fully unlocked Elements in a 3x3 grid. Adjacent Elements in the grid will give you various bonuses, depending on the Elements themselves. You can reassign Elements to the grid anytime.
      
You can place each Element up to 2 times in the grid, and because of the size and shape of the grid, the maximum amount of bonuses you can have is 12. Bonuses are multiplicative with each other.

Some bonus effects may show up as "???". If that is the case, it's because you haven't yet unlocked what the effect does. For example, if an effect would give +10% Droplet of Healing spell power, but you hadn't unlocked the spell Droplet of Healing, it would appear as "???". In addition, sometimes, a pair of Elements may not provide a bonus, or it might give a bonus with negative effects. Optimize your grid so that you avoid these negative effects!

You have the ability to set up 8 grids in total. However, only the active grid will provide its bonuses at any time. You can swap between grids quickly either from the Synchro screen, or from the Exploration screen, under the Strategy tab.

Some grid ideas:

* You can easily obtain 4 times of a single boost by creating a 2x2 square within the grid where the top-left and bottom-right are the same Element, and the top-right and bottom-left are another Element.
* Fire+Earth is a good combination for melee builds. Water+Air is a good combination for offensive spellcasters.

When fully unlocking Elements, they will become usable in the Synchro grid. Revisit your set-up occasionally to see what new combinations you may find!`),
  });
  Helps.register({
    id: "retirement",
    category: HelpCategory.ScreensOverview,
    title: t("Retirement"),
    order: 1200,
    isVisible(state) {
      return isRetirementUnlocked(state);
    },
    description:
      t(`:retirement:**Retirement** is one of the most powerful features in the game. In essence, retiring allows you to restart the game from the beginning, including choosing a new primary Element. However, there are strong benefits to retirement which are detailed below.
    
When you retire, the level your primary Element is currently at is permanently recorded. This level is called the **Maximum Primary Level**, or **MPL**. The MPL for an Element can never go down - if you re-select an Element as your primary and then retire again without reaching your previous MPL, you will keep the previous value.

After you retire, you will get a **Global Experience Multiplier**. This multiplier is very important, as it can vastly improve the effectiveness of studying, letting you unlock stronger spells much, much quicker.

In addition, there is a similar **Global Production Multiplier** which will affect all resource production you have from Channeling, furniture and other features in the game. This includes Mana, which means after retirement it should be much easier to cast stronger spells or cast spells more often. This also affects loot.

Both of the multipliers above are determined based on two factors. The first is the maximum MPL you have ever achieved, regardless of Element. This is the main factor impacting the two multipliers. The second is the sum of all the MPLs you have across all the Elements. This is a secondary factor and it will impact the multipliers, but not as much. Because of this, it's recommended to **pick a new Primary Element when retiring** if possible, to maximize the bonuses.

One last benefit from retirement is related to Storylines. Any **benefits acquired from Storylines will persist through retirements**, meaning progress will become faster. In addition, some Storylines only provide their benefit after you retire once - retiring is encouraged in order to get the full benefit in those cases.

Deciding when to retire is very important. One suggestion is to retire if progress has gotten slower or achieving the next Goal (see: **Home** help section) appears impossible. Another is to retire if the research and production multipliers will substantially increase, or if you complete a Storyline with powerful effects that only appear after retirement. For the first retirement *only*, it is usually a good idea to retire if your new Global Experience Multiplier will be above 2.5x (i.e. do not retire right away after unlocking Retirement).`),
  });
  Helps.register({
    id: "compendium",
    category: HelpCategory.ScreensOverview,
    title: t("Compendium"),
    order: 1210,
    isVisible(state) {
      return BookOfMonsters.isCompleted(state);
    },
    description:
      t(`The :compendium:**Compendium** is a resource that you can use to aid your quest. It contains two separate areas: *Items* and *Bestiary*. **Items** is essentially a list of all the items you have ever acquired, together with their basic effect and a small breakdown of where you could acquire them from. **Bestiary**, on the other hand, is a list of all the enemies you have fought throughout your journey. You can find information such as their stats, loot, or encounter rate. In order to see all the information, you may need to defeat an enemy many times, up to 50. The information on the Compendium persists through Retirement.
      
**Tip: If you lose more than twice to a boss, their entry in the Bestiary will include special combat tips from your hired wizard!** If you are stuck on a boss, this information can help you overcome it.`),
  });

  // Game Features
  Helps.register({
    id: "channeling",
    category: HelpCategory.GameFeatures,
    title: t("Channeling"),
    order: 2000,
    isVisible(state) {
      return true;
    },
    description:
      t(`**Channeling** spells are the most basic spell for each Element, and allow you to generate Essences of each Element. Essences are the most basic resources in the game and the building blocks for almost everything else.
      
The amount of Essences you get from Channeling is calculated based on the amount of :mana: spent on the Channeling spell. Channeling spells, unlike other spells, do not have a fixed :mana: cost. Instead, their cost scales with your Max:mana:, and the Essences you get are proportional to this as well. Because of this scaling, you will be using the same Channeling spells throughout the game.`),
  });
  Helps.register({
    id: "timePieces",
    category: HelpCategory.GameFeatures,
    title: t("Time Pieces"),
    order: 2005,
    isVisible(state) {
      return hasWorldFlag(state, "has_seen_offline_progress");
    },
    description:
      t(`If you close the game or are away from it for longer than 10 minutes, you will gain a new resource: **:timepiece:Time Pieces**.
      
Time Pieces let you regain some of the lost time from being away from the game. You can use them in several different ways:

* You can use them to speed up the game by going to the left-side menu and changing the game speed.
* You can use them to instantly warp ahead in time. It is faster than speeding up the game, but uses up more :timepiece:. You can do this at the bottom of the Home screen.
* You can use them to instantly obtain Element exp equivalent to some real world time. You can use this option to (usually) get more exp than warping for the same amount of :timepiece:. You can do this at the bottom of the Study screen.
* If you have unlocked a certain feature, you can also use them to immediately trigger the next random event. This can be useful in some occasions, as it is cheaper than warping. You can do this at the bottom of the Home screen.

You will get exactly 1:timepiece: for each second you spend offline, and you can store up to 100K:timepiece:. They can be a very effective way to speed up progress, so don't hesitate to use them!`),
  });
  Helps.register({
    id: "primaryElement",
    category: HelpCategory.GameFeatures,
    title: t("Primary Element"),
    order: 2010,
    isVisible(state) {
      return isRetirementUnlocked(state);
    },
    description:
      t(`When you begin a new run, you must select a **Primary Element**. Primary Elements:
      
* Get double experience from spellcasting and Studying,
* Require much less experience to level up,
* Require half the :mana: to cast their spells,
* Are the only elements that matter when calculating your Retirement bonus,
* May have other hidden bonuses later in the game.

Overall, your Primary Element will most likely be the highest level out of all your Elements, which - together with the lower :mana: cost - means that your playstyle will be influenced by your choice of Primary Element.

You can choose a new Primary Element by *retiring* and starting a new run.`),
  });
  Helps.register({
    id: "classes",
    category: HelpCategory.GameFeatures,
    title: t("Classes"),
    order: 2020,
    isVisible(state) {
      return CombatClassesStoryline.isCompleted(state);
    },
    description:
      t(`After you begin a new run through Retirement, you will be able to select a **Class**. Classes provide significant bonuses to various aspects of the game that are specific to the run.
      
When you begin, you will have access to three classes:

* The *Battlemage* class gets a bonus to :attack: and :defense:. It makes Exploration and Combat much easier.
* The *Spellslinger* class gets a significant bonus to :mana: production, letting you cast more spells.
* The *Builder* class gets a bonus to :land: obtained, letting you have more furniture at once in your Home.

You suspect there might be other classes out there... but you're not sure how to find them.`),
  });
  Helps.register({
    id: "turningOffFurniture",
    category: HelpCategory.GameFeatures,
    title: t("Turning off Furniture"),
    order: 2025,
    isVisible(state) {
      return manaSteamers.hasEverBeenPurchased(state);
    },
    description:
      t(`Some Furniture, like Mana Steamers, will consume resources or have some other detrimental effect in order to provide their benefit. To avoid demolishing your Furniture if you have built too many, or if you are facing a temporary situation, you can **turn off** some of them at will.
      
To turn off Furniture, you can do so from the Home screen. If it can be turned off, expanding the Furniture will let you turn it off and on. If a Furniture can be turned off, it will display its count as *(3/5)* or so, where 3 is the amount that are turned on, and 5 is the total amount that are built.

*Note that turning off Furniture does not prevent it from using up your :land:Land.* If you are keeping your Furniture off for a long time, consider demolishing it so that you can build something else instead.`),
  });
  Helps.register({
    id: "partiallyUnlocked",
    category: HelpCategory.GameFeatures,
    title: t("Partially Unlocked Elements"),
    order: 2030,
    isVisible(state) {
      return isDungeonFloorEverCompleted(state, SewerEntrance);
    },
    description:
      t(`Sometimes, from observation of the world around you, you will figure out that **entirely unknown Elements** exist! However, your intuitive, basic knowledge, without any reference material, makes it much more difficult to use their spells or level them up.
      
Partially unlocked Elements:

* Will be much harder (although possible) to Channel,
* Will require a lot more Exp to level up,
* Will not persist through Retirement.

If you find an Element and partially unlock it, it is usually suggested to get some levels on it, even if it's difficult. It might help you gain an edge and get further in progression...`),
  });

  // Wizards

  Helps.register({
    id: "wizardsOverview",
    category: HelpCategory.Wizards,
    title: t("Overview"),
    order: 4000,
    isVisible(state) {
      return isAutocastEverUnlocked(state);
    },
    description: t(
      `The :wizards:**Wizards** feature lets you give instructions to your hired Wizards, who will cast spells as you ask - effectively letting you automate a large part of the game!

In order to automate the casting of a spell, you will need to assign at least one Wizard to it. The Wizards you assign to the spell will generate :wizardpower:Wizard Power, filling a gauge on the spell's rule - and when it's completely full, the spell will automatically cast!

You can assign Wizards to a spell by opening its details in the Spell menu. When a Spell has Wizards assigned to it, it will have a (A) next to its name, signaling that it's being Automated. You can also have Wizards assigned to Transmutation spells from the Transmute screen.

When unlocking the feature, you begin with a single Wizard. However, you can get more by building *Wizard Chambers*, a type of furniture in your :home:Home.`,
    ),
  });
  Helps.register({
    id: "wizardsLayout",
    category: HelpCategory.Wizards,
    title: t("Screen Layout"),
    order: 4010,
    isVisible(state) {
      return isAutocastEverUnlocked(state);
    },
    description: t(
      `The Wizards screen is divided into three areas.

The main area in the center shows a list of rules: they are the instructions that you are giving to your Wizards. Each rule represents a spell, which could be either a normal or a Transmutation spell. You can assign or remove Wizards from the rules here, and you can reorder them for readability if desired. You can also pause the execution of certain rules temporarily.

The area at the top lets you control Wizard Strategies (also called "Loadouts"), which allows you to save a strategy and reuse it later. This is useful especially if your :mana: or :wizardpower: is limited and you would like to create set-ups that focus on different areas of the game.

Finally, the area at the bottom shows summary statistics about your current instructions.`,
    ),
  });
  Helps.register({
    id: "wizardsCaveats",
    category: HelpCategory.Wizards,
    title: t("Caveats"),
    order: 4020,
    isVisible(state) {
      return isAutocastEverUnlocked(state);
    },
    description: t(
      `Your Wizards will only cast a spell when the :wizardpower: for a rule is full. However, there will be times where even if it is full, wizards will not cast spells:
      
If you are unable to cast the spell yourself for any reason (for example, if your :mana: is not enough) the Wizards will not cast the spell.

However, **there is an extra hidden condition** - for many spells, your Wizards will only cast the spell when they deem it "worthwhile", often saving you :mana:. For example, Channeling spells will not be cast if your storage for that elemental essence is full. Or spells that grant a buff may not be cast until the buff's remaining time is less than two seconds. Other spells may have other caveats.`,
    ),
  });
  Helps.register({
    id: "wizardsPermaCast",
    category: HelpCategory.Wizards,
    title: t("Perma-Cast"),
    order: 4030,
    isVisible(state) {
      return permacastStoryline.isBonusActive(state);
    },
    description: t(
      `The Perma-Cast Storyline reward allows your wizards to *Perma-Cast* a spell. Perma-Casting a spell essentially makes your Wizards cast the spell again, indefinitely, as fast as possible or as soon as it's estimated to be needed again. Instead of draining your :mana: all at once at the time the spell is cast, your :mana: will instead be drained over time.
      
You can set a spell to Perma-Cast from each individual spell's details. Not every spell is available for Perma-Cast: usually, they need to either have a cooldown or grant a buff or a debuff to be Perma-Cast eligible.

Perma-Cast's ease of setup is one of its biggest advantages, and is especially good for buffs that you always want to have on, such as Enchant Mana Spouts. When a buff or debuff is caused by a spell you have on Perma-Cast, it will be displayed with a (P) on any lists of buffs or debuffs, and it will show towards the bottom of the list so that you don't need to worry about its presence.

Another, more subtle advantage of Perma-Cast is that Wizards assigned to Perma-Cast spells will pool together their :wizardpower:, so you will likely need less Wizards than if you had individual rules for all of them.

The Perma-Cast rule will group together all spells you have decided to Perma-Cast, and it will always display at the top in the Wizards screen. Wizard Strategies will also include any spells you may have under your Perma-Cast rule.

If your :mana: or :wizardpower: generation is not large enough to sustain all your Perma-Cast spells, your Wizards will prioritize those that are "cheaper". However, despite this, it can still cause problems if it isn't resolved. If this is happening, your Wizards menu icon will change, letting you know that there is an unsolved issue.`,
    ),
  });

  // Exploration

  Helps.register({
    id: "explorationOverview",
    category: HelpCategory.Exploration,
    title: t("Overview"),
    order: 5000,
    isVisible(state) {
      return isExplorationEverUnlocked(state);
    },
    description: t(
      `Your pursuit for the Philosopher's Stone will take you through many dangerous places. What will you find?
      
:exploration:**Exploration** is a key feature of the game and easily the most complex. When you explore, you will find enemies that you will need to engage in combat with (and defeat). Defeating enough enemies on an area will let you fight against the boss of that area. *Challenging ever-more-difficult bosses and defeating them is the main way to make progress in Magic Research 2.*

First, you need to select an area to explore. At the beginning, you will only have a single area unlocked, but more will show up as you make progress in the game.

After you select an area, the screen will change. You will explore for a short period of time, and then you will engage in combat against an enemy. In combat, both you and your enemy attack automatically, dealing damage to each other until a combatant's :hp:HP drops to zero. When that happens, the combatant is defeated.

If you are victorious, you will immediately start exploring again. If, instead, you are defeated, you will first wait for your :hp: to be fully recovered before exploring again. **There is no penalty for losing in combat, except for the time it takes to heal back to full.**`,
    ),
  });
  Helps.register({
    id: "explorationUI",
    category: HelpCategory.Exploration,
    title: t("Exploration UI"),
    order: 5010,
    isVisible(state) {
      return isExplorationEverUnlocked(state);
    },
    description: t(
      `The Exploration screen is divided into roughly two parts.
      
In the top part, you will be able to see the enemy you are currently fighting, their :hp: and their various attacks. If they are affected by a buff or debuff, you will also see it here. Slightly below, you will also see your own :hp: and your automatic attack progress.

In the bottom part, you will see the Exploration Log, a short description that keeps track of the enemies that you have defeated, as well as the loot you have obtained.

There are also four buttons in the bottom, which let you interact with combat in various ways:

* The "Items" tab allows you to access your pouch. You can also let the game automatically use most of your pouch items for you.
* The "Spells" tab allows you to cast a subset of spells that is particularly useful for Exploration. You can also add spells to your Quickbar by expanding them in this tab.
* The "Strategy" tab allows you to change your Wizard strategy (see "Screen Layout" under the "Wizards" category), switch between equipment sets, or control some aspects of how Exploration works, such as telling the game to wait until you fully recover before you Explore again.
* Finally, the "Quickbar" tab is the best way to react to a heated battle. You can set key spells to your Quickbar and use them without having to look through the menus, which is important in difficult battles. The tab also shows a short list of buffs and debuffs on the right.`,
    ),
  });
  Helps.register({
    id: "explorationPouch",
    category: HelpCategory.Exploration,
    title: t("Pouch Items"),
    order: 5020,
    isVisible(state) {
      return isExplorationEverUnlocked(state);
    },
    description: t(
      `Pouch Items are an important category of *Equipment*. They are usually potions or fruit that recover your :hp: or grant you a buff for some time. You can equip pouch items from the Inventory screen.

**Pouch Items are not consumable, and they will not disappear when used.** They are considered pieces of equipment. You start the game with four pouch item slots, and you can have multiples of the same item in those slots. For example, you can fill your pouch with four :cherry:Cherry.
      
Pouch items can be used only during combat, and each one will have a certain amount of uses. Entering a new combat will refresh the pouch items and restore their uses.

Most pouch items will be used automatically during combat unless you disable this behavior in the "Items" tab in Exploration. Healing items will typically be used if your :hp: is dangerously low, or if you would obtain their full effect. Buffing items will typically be used whenever the buff is not present, which is usually at the beginning of a battle.

Pouch items are key to succeeding in Exploration, and may be your main source of healing during combat for a good portion of the game. Think very carefully about what pouch items to equip before entering combat: you will need to wait until the next combat to use it if you equip a new pouch item while you are already fighting.`,
    ),
  });
  Helps.register({
    id: "explorationBasicStats",
    category: HelpCategory.Exploration,
    title: t("Basic Combat Stats"),
    order: 5030,
    isVisible(state) {
      return isExplorationEverUnlocked(state);
    },
    description: t(
      `There are a handful of stats that are specific to Exploration. They are:
      
:hp:HP is short for "Health Points". It represents a combatant's life. If it becomes 0, the combatant will be defeated. Your :hp: slowly regenerates over time. The higher your Max:hp:, the faster it will regenerate. Additionally, your :hp: will regenerate much faster when outside of combat.

:attack:Attack is the basic damage you would deal with your automatic attack. The damage you actually deal to an enemy will usually be lower than this, as it is reduced by their :defense:Defense stat.

:attackDelay:Attack Delay is the amount of time between automatic attacks. The lower it is, the better.

:defense:Defense reduces the damage you take from enemy attacks. Generally, two points of :defense: will reduce damage by 1. However, if your :defense: is much higher than the incoming :attack:, the reduction will slowly become less. You can usually reduce the damage from an attack to 1 if your :defense: is about *four times* the incoming :attack:.

Here is an example of how :defense: reduces the damage from a 50:attack::

* With 0:defense:: it would deal 50 damage
* With 20:defense:: it would deal 40 damage
* With 50:defense:: it would deal 25 damage
* With 100:defense:: it would deal 9 damage
* With 150:defense:: it would deal 4 damage
* With 200:defense:: it would deal 1 damage`,
    ),
  });
  Helps.register({
    id: "explorationDodgeAccuracy",
    category: HelpCategory.Exploration,
    title: t("Dodge and Accuracy"),
    order: 5040,
    isVisible(state) {
      return hasEverHadFlag(state, Flag.SewersUnlocked);
    },
    description: t(
      `As you make your way into the Sewers, two more stats will become important in Exploration. They are:
      
:dodge:Dodge allows you (or an enemy) to evade attacks entirely. :dodge: starts at 100, and can be increased or decreased by many things, including equipment and buffs. The higher your :dodge: is, the bigger the likelihood you will evade an attack.

:accuracy:Accuracy, on the other hand, determines the likelihood you will be able to hit an enemy. Like :dodge:, it starts at 100, and can be increased or decreased by many things. If your :accuracy: is higher than or equal to the enemy's :dodge:, your attacks will never miss.

If :accuracy: and :dodge: are not displayed in a list of stats, they are implied to be exactly 100.

The hit rate of an attack depends on the proportion between the attacker's :accuracy: and the defender's :dodge:. Here's an example (assuming 100:accuracy:, the default):

* With 100:dodge: or less: it would always hit
* With 120:dodge:: it would have a 91% chance of hitting
* With 150:dodge:: it would have a 74% chance of hitting
* With 200:dodge:: it would have a 50% chance of hitting
* With 300:dodge:: it would have a 18% chance of hitting
* With 400:dodge:: it would have a 0% chance of hitting

**Note that :dodge: and :accuracy: also affect spells that deal damage!**`,
    ),
  });
}
