import memoize from "sonic-memoize";
import { t } from "../../utils/I18n";
import { ActionEffect } from "../action/Action";
import { cacheBooleanTrueOnly } from "../Caching";
import { AttackTarget } from "../exploration/AttackTarget";
import { clearCombatStatCache } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { meetsRequirements, SpellElement } from "../spells/Elements";
import { clearResearcherExpCache } from "../study/Study";
import { grantTemporaryEffect } from "../temporaryeffects/TemporaryEffects";
import { clearCalculatedIncomeCache } from "../timetick/IncomeOverTime";
import {
  applyTransformations,
  clearTransformationCache,
  explainTransformationsText,
  TransformationTag,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { ritualWeakness } from "./RitualWeakness";

export abstract class Ritual {
  constructor() {}

  abstract getId(): string;
  abstract getBaseName(): string;
  abstract getDescription(state: GameState): string;
  abstract getEffect(state: GameState): string;
  abstract getBaseCorruptionCost(): number;

  getDisplayName(state: GameState): string {
    const baseName = this.getBaseName();
    return !this.isActive(state)
      ? baseName
      : t("{{ritualName}} (Active)", { ritualName: baseName });
  }

  protected getBaseRitualEffects(): Record<string, ActionEffect> {
    return {};
  }

  _costTags = [
    TransformationTags.Corruption,
    TransformationTags.PerRitualCost,
  ].concat(this.getTags() as TransformationTags[]);

  getCorruptionCost(state: GameState): number {
    return applyTransformations(
      this._costTags,
      state,
      this.getBaseCorruptionCost(),
      { ritualId: this.getId() },
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Death: this.getDeathLevelRequirement(),
    };
  }

  abstract getDeathLevelRequirement(): number;

  isVisible(state: GameState): boolean {
    return cacheBooleanTrueOnly(
      state,
      "ritualVisibility." + this.getId(),
      (s) => {
        const elementRequirements = this.getLevelRequirements();
        const isVisible = meetsRequirements(s, elementRequirements);
        return isVisible;
      },
    );
  }

  turnOn(state: GameState): GameState {
    if (!this.isVisible(state)) {
      return state;
    }

    state.run.activeRituals[this.getId()] = true;
    state = clearTransformationCache(
      new Set([TransformationTags.Corruption]),
      state,
    );
    state = grantTemporaryEffect(ritualWeakness.getId(), 60, {
      target: AttackTarget.Player,
    })(state);
    state = clearCombatStatCache(state);
    state = clearCalculatedIncomeCache(state);
    state = clearResearcherExpCache(state);
    return state;
  }

  turnOff(state: GameState): GameState {
    state.run.activeRituals[this.getId()] = false;
    state = clearTransformationCache(
      new Set([TransformationTags.Corruption]),
      state,
    );
    state = clearCombatStatCache(state);
    state = clearCalculatedIncomeCache(state);
    state = clearResearcherExpCache(state);
    return state;
  }

  isActive(state: GameState): boolean {
    return state.run.activeRituals[this.getId()] ?? false;
  }

  getTags(): TransformationTag[] {
    const tags = [TransformationTags.Ritual, this.getId()];
    return tags;
  }

  getCachedBaseRitualEffects = memoize(() => this.getBaseRitualEffects());

  tagsByEffect: Record<string, string[]> = {};
  getTagsByEffect(key: string, baseEffect: ActionEffect): string[] {
    if (this.tagsByEffect?.[key] == null) {
      this.tagsByEffect[key] = [
        ...this.getTags(),
        ...(baseEffect.tags ?? []),
        TransformationTags.RitualEffect,
        key,
      ];
    }
    return this.tagsByEffect[key];
  }

  ritualEffectParams = memoize(() => ({ ritual: this }));

  getRitualEffects(state: GameState, onlyId?: string): Record<string, number> {
    const baseEffects = this.getCachedBaseRitualEffects();
    const finalEffects: Record<string, number> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = applyTransformations(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.ritualEffectParams(),
      );
    }
    return finalEffects;
  }

  ritualEffectExplanationParams = memoize((baseEffect: ActionEffect) => ({
    ritual: this,
    unit: baseEffect.unit,
    valueType: baseEffect.valueType,
  }));

  getRitualEffectExplanations(
    state: GameState,
    onlyId?: string,
  ): Record<string, string> {
    const baseEffects = this.getCachedBaseRitualEffects();
    const finalEffects: Record<string, string> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = explainTransformationsText(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.ritualEffectExplanationParams(baseEffect),
      );
    }
    return finalEffects;
  }
}
