import { formatNumber } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { ActionEffect } from "../../../action/Action";
import { AttackTarget } from "../../../exploration/AttackTarget";
import {
  clearCombatStatCache,
  CombatStat,
} from "../../../exploration/CombatStats";
import { GameState } from "../../../GameState";
import { Resource } from "../../../Resources";
import { SpellElement } from "../../../spells/Elements";
import { TemporaryEffect } from "../../../temporaryeffects/TemporaryEffect";
import {
  getTemporaryEffectData,
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "../../../temporaryeffects/TemporaryEffects";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../../transformation/TransformationTags";
import { ItemParams } from "../../Item";
import { EquipmentTransmutationSpell } from "../../transmute/EquipmentTransmutationSpell";
import { registerTransmutationSpellAndItem } from "../../transmute/TransmutationSpell";
import { EquippableItem } from "../EquippableItem";
import { BuffingPouchItem } from "./BuffingPouchItem";

class PotionOfVitalityTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "potionOfVitality";
  }
  getDisplayName(): string {
    return t("Potion of Vitality");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("+{{magnitude}}% :hp: regeneration in combat", {
      magnitude: formatNumber(temporaryEffectData.params.magnitude * 100),
    });
  }
  getIcon() {
    return require("./potionOfVitality.png");
  }
}

const temporaryEffect = new PotionOfVitalityTemporaryEffect();

class PotionOfVitality extends BuffingPouchItem {
  getTemporaryEffect(): TemporaryEffect {
    return temporaryEffect;
  }

  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t("A magical concoction that improves your regeneration powers.");
  }

  protected getBaseItemEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 0.5,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        valueType: TransformationValueType.Percent,
      },
      duration: {
        value: 300,
        tags: [TransformationTags.TemporaryEffectDuration],
      },
    };
  }

  getEffect(state: GameState, params: ItemParams): string | undefined {
    const effects = this.getItemEffects(state, params);
    const explanations = this.getItemEffectExplanations(state, params);

    return t(
      "^+{{magnitude}}%^<{{explanation}}> :hp: regeneration in combat for ^{{duration}}^<{{durationExplanation}}> sec",
      {
        magnitude: formatNumber(effects.magnitude * 100),
        explanation: explanations?.magnitude,
        duration: formatNumber(Math.floor(effects.duration)),
        durationExplanation: explanations?.duration,
      },
    );
  }

  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 400;
  }
}

const potionOfVitality = new PotionOfVitality();

class TransmutePotionOfVitality extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return potionOfVitality;
  }

  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Water: 10,
    };
  }

  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 300,
        WaterEssence: 3000,
      },
      items: {
        elementalShardWater: 15,
        slimeDrop: 10,
      },
    };
  }

  getElement(): SpellElement {
    return SpellElement.Water;
  }
}

const transmutePotionOfVitality = new TransmutePotionOfVitality();

export function loadPotionOfVitality() {
  TemporaryEffects.register(temporaryEffect);
  registerTransmutationSpellAndItem(transmutePotionOfVitality);

  registerTransformation(
    [[AttackTarget.Player, CombatStat.HPRegen]],
    temporaryEffect.getId(),
    temporaryEffect.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(state, temporaryEffect.getId(), AttackTarget.Player)
        ? getTemporaryEffectData(
            state,
            temporaryEffect.getId(),
            AttackTarget.Player,
          ).params.magnitude + 1.0
        : 1.0,
  );
  registerTemporaryEffectListener(temporaryEffect.getId(), (state, effect) => {
    if (effect.id == temporaryEffect.getId()) {
      state = clearCombatStatCache(
        state,
        AttackTarget.Player,
        CombatStat.HPRegen,
      );
    }
    return effect;
  });
}

export {
  temporaryEffect as PotionOfVitalityTemporaryEffect,
  potionOfVitality as PotionOfVitality,
  transmutePotionOfVitality as TransmutePotionOfVitality,
};
