import memoize from "sonic-memoize";
import { isDemo } from "../Demo";
import { DataStore } from "../generic/DataStore";
import { Storyline } from "./Storyline";

export const Storylines = new DataStore<Storyline>();

function _getSortedStorylines(): Storyline[] {
  return Storylines.getAll().sort((a, b) => a.order - b.order);
}

export const getSortedStorylines = memoize(_getSortedStorylines);

export const getTotalStorylineCount = () =>
  Storylines.getAll().length - (isDemo() ? 0 : 1);

export const storylineHintUnlockChecker = (state) => {
  const storylines = Storylines.getAll().filter((storyline) =>
    storyline.shouldRevealHint(state),
  );
  if (storylines.length > 0) {
    storylines.forEach((storyline) => {
      state = storyline.revealHint(state);
    });
  }
  return state;
};
