import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { ActionEffect } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { AttackTarget } from "../../exploration/AttackTarget";
import { calculatePlayerMaxHP } from "../../exploration/CombatStats";
import {
  getCurrentPlayerHP,
  modifyPlayerCurrentHP,
} from "../../exploration/Exploration";
import { pushCombatActionResult } from "../../exploration/ExplorationActionResult";
import { isHealingPossible } from "../../temporaryeffects/CommonTemporaryEffects";
import { getSecondsPlayed } from "../../timetick/TotalTimePlayed";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";
import { getStandardRangeEffectText } from "../Utils";

class DropletOfHealing extends CombatSpellBase {
  getId(): string {
    return "dropletOfHealing";
  }

  getSpellName(): string {
    return t("Droplet of Healing");
  }

  getElement(): SpellElement {
    return SpellElement.Water;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.HEALING;
  }

  getBaseManaCost(state: GameState): number {
    return 900;
  }

  getBaseCooldown(): number {
    return 10.0;
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Conjure a small droplet of curative water. Slightly heals wounds.",
    );
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      average: {
        value: 25,
        tags: [TransformationTags.ActionMagnitude],
        unit: ":hp:",
      },
      variance: {
        value: 3,
        tags: [TransformationTags.ActionVariance],
        unit: ":hp:",
      },
    };
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanation = this.getActionEffectExplanations(state, "average");
    return t("+{{effect}}:hp:", {
      effect: getStandardRangeEffectText(
        effects.average,
        effects.variance,
        explanation.average,
      ),
    });
  }

  doSpellAction(state: GameState): GameState {
    const effects = this.getActionEffects(state);
    const healAmt =
      effects.average + (Math.random() - 0.5) * effects.variance * 2;
    state = modifyPlayerCurrentHP(state, healAmt, this.getSpellName());
    const actionResult = {
      id: Math.random().toString(),
      damage: -healAmt,
      isCrit: false,
      isHit: true,
      time: getSecondsPlayed(state),
      source: AttackTarget.Player,
      target: AttackTarget.Player,
      tags: this.getTags(),
    };
    state = pushCombatActionResult(state, actionResult);
    return state;
  }

  canUseOutsideOfCombat(): boolean {
    return true;
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Water: 8,
    };
  }

  getBaseAutocastPointsRequirement(): number {
    return 40;
  }

  isCastingRecommended(state: GameState): boolean {
    return (
      getCurrentPlayerHP(state) < calculatePlayerMaxHP(state) * 0.99 &&
      isHealingPossible(state, AttackTarget.Player)
    );
  }
}

export default new DropletOfHealing();
