import moment from "moment";
import React, { useMemo } from "react";
import { ScrollView, View } from "react-native";
import { getFamiliarMessageLog } from "../../backend/familiars/Familiar";
import { t } from "../../utils/I18n";
import { Markdown } from "../utility/Markdown";
import { useGameState } from "../utility/useGameState";

export function FamiliarMessageLog(props: { familiarId: string }) {
  const state = useGameState();
  const messageLog = getFamiliarMessageLog(state, props.familiarId);
  return useMemo(
    () => (
      <ScrollView>
        {messageLog.length === 0 ? (
          <View key="noActivity">
            <Markdown>{t("*No activity yet!*")}</Markdown>
          </View>
        ) : (
          messageLog.map((logItem) => {
            const key: string = logItem.id;
            return (
              <View key={key}>
                <Markdown
                  accessibilityLabel={
                    logItem.accessibleMessage ?? logItem.message
                  }
                >
                  {logItem.realTimestamp
                    ? t("[{{time}}] {{message}}", {
                        time: moment(logItem.realTimestamp).format("h:mm:ss A"),
                        message: logItem.message,
                      })
                    : logItem.message}
                </Markdown>
              </View>
            );
          })
        )}
      </ScrollView>
    ),
    [messageLog.map((logItem) => logItem.id).join()],
  );
}
