import React, { useMemo } from "react";
import { Image, ImageSourcePropType, StyleSheet, View } from "react-native";
import { sanitizeImageSourceForElectron } from "../utility/icons/IconUtils";

export function FamiliarSpriteTile(props: {
  sprite?: ImageSourcePropType;
  showDead?: boolean;
  showUndead?: boolean;
}) {
  return useMemo(
    () => (
      <View style={styles.tile}>
        {props.sprite && props.showUndead && (
          <Image
            resizeMode="contain"
            source={sanitizeImageSourceForElectron(props.sprite)}
            style={[
              styles.image,
              {
                tintColor: "#444",
              },
            ]}
          />
        )}
        {props.sprite && (
          <Image
            resizeMode="contain"
            source={sanitizeImageSourceForElectron(props.sprite)}
            style={[
              styles.image,
              {
                opacity: props.showUndead ? 0.15 : props.showDead ? 0.25 : 1.0,
                position: "absolute",
              },
            ]}
          />
        )}
      </View>
    ),
    [props.sprite, props.showDead, props.showUndead],
  );
}

const styles = StyleSheet.create({
  tile: {
    height: 24,
    width: 24,
    justifyContent: "center",
    alignItems: "stretch",
  },
  image: {
    height: 22,
    width: 22,
  },
});
