import { createTempState, GameState, GameStateTransform } from "./GameState";
import { SpellElement } from "./spells/Elements";

export function cacheByTickCount(
  state: GameState,
  key: string,
  valueFn: (state: GameState) => any,
  readCacheOnly?: boolean,
) {
  if (state.isFake) {
    return valueFn(state);
  }
  const tickCount = state.global.tickCount;
  const cache = state.temp.cache;
  const oldValue = cache.get(key);
  if (!oldValue || (!readCacheOnly && oldValue[0] != tickCount)) {
    const newValue = valueFn(state);
    cache.set(key, [tickCount, newValue]);
    return newValue;
  }
  return oldValue[1];
}

export function cacheByTickCountElementLevel(
  state: GameState,
  key: SpellElement,
  valueFn: (state: GameState) => any,
  readCacheOnly?: boolean,
) {
  if (state.isFake) {
    return valueFn(state);
  }
  const tickCount = state.global.tickCount;
  const cache = state.temp.elementLevelCache;
  const oldValue = cache[key];
  if (!oldValue || (!readCacheOnly && oldValue[0] != tickCount)) {
    const newValue = valueFn(state);
    cache[key] = [tickCount, newValue];
    return newValue;
  }
  return oldValue[1];
}

