/* 
Base colors:

Fire: #cb1e1e
Earth: #b66336
Water: #009ce9
Air: #89ab83
Poison: #7e18ef
Mind: #00dddd
Life: #95AB6C
Electric: #fbc02d
Time: #C21872
Space: #00308f

For Death: 
Dark mode: grab Fire, desaturate every color, and decrease lightness by 50%
Light mode: same thing

For Holy:
Dark mode: grab Fire, desaturate every color
Light mode: same thing
*/
function generateDeathColor(fireColor: string, isDark: boolean) {
  const baseColor = color(fireColor);
  const grayColor = baseColor.grayscale();
  return grayColor.darken(isDark ? 0.3 : 0.2).hexa();
}
function generateHolyColor(fireColor: string, isDark: boolean) {
  const baseColor = color(fireColor);
  const grayColor = baseColor.grayscale();
  return grayColor.lighten(isDark ? 0.3 : 0.4).hexa();
}
