const getThemeByElementAndDarkMode = memoize(
  getThemeByElementAndDarkModeUnmemoized,
);

export const getThemeWithOverriddenOnError = memoize((theme, onError) => {
  const newTheme = clone(theme);
  newTheme.colors.onError = onError;
  return newTheme;
});

export { getThemeByElementAndDarkMode };
