import moment from "moment";
import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet } from "react-native";
import { FlatList } from "react-native-gesture-handler";
import { List } from "react-native-paper";
import { EndingStats } from "../../backend/GameState";
import { addUICommand } from "../../backend/UICommands";
import { Challenges } from "../../backend/challenges/Challenges";
import { Endings } from "../../backend/ending/Endings";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { useGameState } from "./useGameState";

function EndingHistoryRow(props: {
  endingData: EndingStats;
  onPress: () => void;
}) {
  const endingData = props.endingData;
  const ending = Endings.getById(endingData.endingId);
  const timestamp = moment(endingData.timestamp).format("Y-MM-DD h:mm A");
  const worldStats =
    endingData.worldsCreated > 0
      ? t(
          "{{sheerPower}}/{{storylineOracle}}/{{blazingSpeed}} - D{{worldDifficulty}}",
          {
            sheerPower: formatNumber(endingData.sheerPowerLevel),
            storylineOracle: formatNumber(endingData.storylineOracleLevel),
            blazingSpeed: formatNumber(endingData.blazingSpeedLevel),
            worldDifficulty: formatNumber(endingData.worldDifficultyLevel),
          },
        )
      : "";
  const challengeStats =
    endingData.challengeId != null
      ? Challenges.getById(endingData.challengeId).getName()
      : "";
  const extraStats =
    worldStats != "" || challengeStats != ""
      ? t(" ({{extraStatsText}})", {
          extraStatsText: [worldStats, challengeStats]
            .filter((text) => text != "")
            .join(t("; ")),
        })
      : "";
  const title = t("{{ending}}{{extraStats}}", {
    ending: ending.title,
    extraStats,
  });
  const description = timestamp;
  return (
    <List.Item
      title={title}
      description={description}
      onPress={props.onPress}
      right={() => <List.Icon icon="chevron-right" />}
    />
  );
}

export default function EndingHistoryContainer(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const endings = state.global.endingsHistory ?? [];

  const renderItem = useCallback(
    (params: { item: EndingStats; index: number }) => (
      <EndingHistoryRow
        endingData={params.item}
        onPress={() =>
          stateRef.current.apply((s) => {
            s = addUICommand(
              s,
              "endingHistoryOverride:" +
                (endings.length - 1 - params.index).toString(),
            );
            s = addUICommand(s, "goToEnding");
            return s;
          })
        }
      />
    ),
    [],
  );

  return useMemo(() => {
    if (endings.length === 0) {
      return null;
    }
    return (
      <FlatList data={endings?.toReversed?.() ?? []} renderItem={renderItem} />
    );
  }, [endings.length]);
}

const styles = StyleSheet.create({});
