import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { TransformationType } from "../transformation/Transformation";
import { TemporaryEffect } from "./TemporaryEffect";
import { TemporaryEffectData, TemporaryEffects } from "./TemporaryEffects";

class Shapeshifted extends TemporaryEffect {
  getId(): string {
    return "shapeshifted";
  }
  getDisplayName(): string {
    return t("Shapeshifted");
  }
  getIcon() {
    return require("./shapeshifted.png");
  }
  isBeneficial(): boolean {
    return true;
  }
  isCommonBuffEnabled(): { stat: CombatStat; type: TransformationType }[] {
    return valuesOf(CombatStat)
      .map((stat) => [
        { stat, type: TransformationType.Addition },
        { stat, type: TransformationType.Multiplier },
      ])
      .flat();
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Temporarily shapeshifted into {{what}}. Various stats altered.", {
      what: temporaryEffectData.params.what,
    });
  }
}

export const shapeshifted = new Shapeshifted();

export function loadShapeshifted() {
  TemporaryEffects.register(shapeshifted);
}
