import { formatNumber } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { Building } from "../../../buildings/Building";
import { getBuildingAmount } from "../../../buildings/Buildings";
import { essenceCatalyst } from "../../../buildings/catalysts/EssenceCatalyst";
import { ManaSpout } from "../../../buildings/mana/ManaSpout";
import { SaleStall } from "../../../buildings/SaleStall";
import { essenceStorage } from "../../../buildings/storages/EssenceStorage";
import { ManaShard } from "../../../buildings/storages/ManaShard";
import SewerEntrance from "../../../exploration/dungeons/sewers/SewerEntrance";
import {
  addEnemyDeathListener,
  getSuccessfulExplorationsForFloor,
} from "../../../exploration/Exploration";
import { Flag } from "../../../Flag";
import { setFlag } from "../../../Flags";
import { registerPostRetirementListener } from "../../../Retire";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

export const FURNITURE_PRESERVATION_OPTIONS: {
  furniture: Building;
  amount: number;
}[] = [
  { furniture: ManaSpout, amount: 12 }, // 24 land
  { furniture: ManaShard, amount: 8 }, // 16 land
  { furniture: essenceStorage, amount: 5 }, // 25 land
  { furniture: essenceCatalyst, amount: 2 }, // 20 land
  { furniture: SaleStall, amount: 3 }, // 12 land
];

export function loadFurniturePreservation() {
  function createStorylineForOption(option: {
    furniture: Building;
    amount: number;
  }) {
    const { furniture, amount } = option;
    return new Storyline(
      "furniturePreservation-" + furniture.getId(),
      t("Furniture Preservation ({{furniture}})", {
        furniture: furniture.getName(),
      }),
      210 + FURNITURE_PRESERVATION_OPTIONS.indexOf(option),
      20,
      (state) =>
        !furniturePreservationEvent.hasTriggered(state)
          ? t("Explore the Sewer Entrance", {
              amount: formatNumber(amount),
              furniture: furniture.getName(),
            })
          : t(
              "This Storyline can be found in the Sewers, but you cannot get it this run",
            ),
      t("Begin with {{amount}} {{furniture}} after retirement", {
        amount: formatNumber(amount),
        furniture: furniture.getName(),
      }),
      (_state) => true,
    );
  }

  const furniturePreservationStorylines: Storyline[] =
    FURNITURE_PRESERVATION_OPTIONS.map((option) =>
      createStorylineForOption(option),
    );

  const furniturePreservationEventBuilder = buildEvent(
    "furniturePreservation",
    t("(Storyline) Furniture Preservation"),
    [EventTag.STORYLINE],
  ).setStorylineIds(
    furniturePreservationStorylines.map((storyline) => storyline.getId()),
  );

  furniturePreservationEventBuilder.message(
    t(
      `While traversing the Sewers, you find a curious item on the floor: a container of some kind of... substance? Gel? Cream? You are not sure exactly what it is. There is a label, however: "Furniture Preserver". You decide to bring it home.
      
But when you show your hired wizard, she exclaims, really excited: "*Furniture Preserver*?! I've been wanting some of that for a very long time! It is so rare and yet so useful! Can I have it? Pretty please...?" She proceeds to look at you with a puppy face.

She then explains herself: like the label says, this substance can be applied to furniture for it to last very, very long, almost indefinitely long.`,
    ),
  );

  const messageBuilder = furniturePreservationEventBuilder.message(
    t(
      `You think about the furniture you already own. There isn't so much of this preserver. You tell your hired wizard that you are planning to use half of it, and that she can have the rest. She bursts in happiness and thanks you, full of joy.
      
And then, you have to think of how you are going to use your half...`,
    ),
  );

  for (let idx in FURNITURE_PRESERVATION_OPTIONS) {
    const option = FURNITURE_PRESERVATION_OPTIONS[idx];
    const storyline = furniturePreservationStorylines[idx];
    const tParams = {
      amount: formatNumber(option.amount),
      furniture: option.furniture.getName(),
    };
    messageBuilder.option(
      t("Preserve {{amount}} {{furniture}}", tParams),
      {
        transform: (state) => storyline.complete(state),
        isVisible: (state) => !storyline.isCompleted(state),
        isEnabled: (state) =>
          getBuildingAmount(state, option.furniture) >= option.amount,
      },
      "end-" + option.furniture.getId(),
    );
    furniturePreservationEventBuilder
      .message(
        t(
          `You carefully apply the preserver to your furniture. As you do, it begins to glow with a strange, yet gentle blue light. It was actually a *magical* product?
      
Finishing up, you give the remaining preserver to your hired wizard. She thanks you, and proceeds to quickly apply it *on herself*. You try to tell her that it's probably not going to work, but she quickly says it's none of your business...

**You have completed the Storyline "Furniture Preservation ({{furniture}})"! In future retirements, you will begin with {{amount}} {{furniture}} already in your Home!**`,
          tParams,
        ),
      )
      .tag("end-" + option.furniture.getId())
      .end();
  }

  messageBuilder.option(t("Decide later")).end();

  for (let idx in FURNITURE_PRESERVATION_OPTIONS) {
    Storylines.register(furniturePreservationStorylines[idx]);
  }

  const furniturePreservationEvent = furniturePreservationEventBuilder.build();

  addEnemyDeathListener(
    furniturePreservationEvent.getId() + "Triggerer",
    (state) => {
      if (
        !furniturePreservationEvent.hasTriggered(state) &&
        furniturePreservationStorylines.some(
          (storyline) => !storyline.isCompleted(state),
        ) &&
        (getSuccessfulExplorationsForFloor(state, "sewerEntrance") >= 8 ||
          SewerEntrance.canFightBoss(state))
      ) {
        return triggerEvent(furniturePreservationEvent)(state);
      }
      return state;
    },
  );

  FURNITURE_PRESERVATION_OPTIONS.forEach((option, idx) => {
    const id = option.furniture.getId();
    const storyline = furniturePreservationStorylines[idx];
    registerPostRetirementListener("furniturePreservation" + id, (state) => {
      if (storyline.isBonusActive(state)) {
        state.run.buildings[id] = {
          current: option.amount,
          turnedOn: option.amount,
        };
      }

      return state;
    });
  });

  registerPostRetirementListener(
    "furniturePreservationAutoUnlockStuff",
    (state) => {
      // Mana Spouts
      if (furniturePreservationStorylines[0].isBonusActive(state)) {
        state = setFlag(Flag.ManaSpoutsUnlocked)(state);
      }
      // Mana Shards
      if (furniturePreservationStorylines[1].isBonusActive(state)) {
        state = setFlag(Flag.ManaShardsUnlocked)(state);
      }
      // Essence Storages
      if (furniturePreservationStorylines[2].isBonusActive(state)) {
        state = setFlag(Flag.StoragesUnlocked)(state);
      }
      // Essence Catalysts
      if (furniturePreservationStorylines[3].isBonusActive(state)) {
        state = setFlag(Flag.CatalystsUnlocked)(state);
      }
      // Market Stalls
      if (furniturePreservationStorylines[4].isBonusActive(state)) {
        state = setFlag(Flag.SaleStallsUnlocked)(state);
      }

      return state;
    },
  );
}
