import { formatMultiplier, formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { ActionEffect, DoActionArgs } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { CombatStat } from "../../exploration/CombatStats";
import { TemporaryEffect } from "../../temporaryeffects/TemporaryEffect";
import {
  TemporaryEffectData,
  TemporaryEffects,
  grantTemporaryEffect,
} from "../../temporaryeffects/TemporaryEffects";
import { TransformationType } from "../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";
import { registerEmpowerEffects } from "../Spells";

class HasteTemporaryEffect extends TemporaryEffect {
  getId() {
    return "haste";
  }

  getDisplayName(): string {
    return t("Haste");
  }

  getIcon() {
    return require("./haste.png");
  }

  isCommonBuffEnabled(): { stat: CombatStat; type: TransformationType }[] {
    return [
      { stat: CombatStat.AttackDelay, type: TransformationType.Multiplier },
    ];
  }

  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    const attackDelayRatio =
      temporaryEffectData.params.commonBuff.AttackDelayMultiplier;
    return t("{{ratio}}:attackDelay:", {
      ratio: formatMultiplier(attackDelayRatio),
    });
  }
}

const temporaryEffect = new HasteTemporaryEffect();
TemporaryEffects.register(temporaryEffect);

const BASE = 0.13637;

class Haste extends CombatSpellBase {
  getId(): string {
    return "haste";
  }

  getSpellName(): string {
    return t("Haste");
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.COMBAT_BUFF;
  }

  getBaseManaCost(state: GameState): number {
    return 700;
  }

  canUseOutsideOfCombat(): boolean {
    return true;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 2.0,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        valueType: TransformationValueType.Log2Divisor,
        base: BASE,
      },
      duration: {
        value: 60.0,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    const effects = this.getActionEffects(state);
    return grantTemporaryEffect(temporaryEffect.getId(), effects.duration, {
      params: {
        commonBuff: {
          AttackDelayMultiplier:
            1.0 / (1 + BASE * Math.max(0, Math.log2(effects.magnitude))),
        },
      },
    })(state);
  }

  getDisplayDescription(state: GameState): string {
    return t("Enchant your body to be able to move faster.");
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    return t(
      "^{{magnitude}}^<{{magnitudeExplanation}}>:attackDelay: for ^{{duration}}^<{{durationExplanation}}> sec",
      {
        magnitude: formatMultiplier(
          1.0 / (1 + BASE * Math.max(Math.log2(effects.magnitude))),
        ),
        magnitudeExplanation: explanations.magnitude,
        duration: formatNumber(effects.duration),
        durationExplanation: explanations.duration,
      },
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Air: 8 };
  }

  protected isEmpowerable(): boolean {
    return true;
  }

  getEmpoweringLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Air: 52 };
  }
}

const haste = new Haste();

registerEmpowerEffects(haste, [TransformationTags.Cost, Resource.Mana], 10);
registerEmpowerEffects(haste, [TransformationTags.TemporaryEffectMagnitude], 2);

export { haste as Haste, temporaryEffect as HasteTemporaryEffect };
