import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ActionEffect } from "../action/Action";
import { GameState } from "../GameState";
import { Storylines } from "../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../transformation/TransformationTags";
import { CombatClass } from "./CombatClasses";

class Builder extends CombatClass {
  getId(): string {
    return "builder";
  }
  getDisplayName(): string {
    return t("Builder");
  }
  protected getBaseClassEffects(): Record<string, ActionEffect> {
    return {
      land: {
        value: 0.12,
        valueType: TransformationValueType.Percent,
      },
      buildingCost: {
        value: 1.5,
        valueType: TransformationValueType.Divisor,
      },
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "A wizard that recognizes furniture is the cornerstone to success, and is able to maximize space in their home - to have even more furniture.",
    );
  }
  getDisplayEffect(state: GameState): string {
    const effects = this.getClassEffects(state);
    const explanations = this.getClassEffectExplanations(state);
    if (Storylines.getByIdNullable("blessedLand")?.isBonusActive?.(state)) {
      return t(
        "^{{buildingCostMultiplier}}^<{{buildingCostExplanation}}> cost of all furniture",
        {
          buildingCostMultiplier: formatMultiplier(
            1.0 / (1.0 + effects.buildingCost),
          ),
          buildingCostExplanation: explanations.buildingCost,
        },
      );
    }
    return t("^{{landMultiplier}}^<{{landExplanation}}>:land:", {
      landMultiplier: formatMultiplier(1.0 + effects.land),
      landExplanation: explanations.land,
    });
  }
}

export const builder = new Builder();

registerTransformation(
  [[TransformationTags.Land, TransformationTags.Cap]],
  builder.getId(),
  builder.getDisplayName(),
  TransformationType.Multiplier,
  (state) =>
    builder.isActive(state) ? 1.0 + builder.getClassEffects(state).land : 1.0,
);

registerTransformation(
  [[TransformationTags.Cost, TransformationTags.Building]],
  builder.getId() + "buildingCost",
  builder.getDisplayName(),
  TransformationType.Multiplier,
  (state) =>
    builder.isActive(state) &&
    Storylines.getByIdNullable("blessedLand")?.isBonusActive?.(state)
      ? 1.0 / (1.0 + builder.getClassEffects(state).buildingCost)
      : 1.0,
);
