import React, { useRef } from "react";
import { StyleSheet, View } from "react-native";
import { EndingStats } from "../backend/GameState";
import { Endings, getEndingData } from "../backend/ending/Endings";
import { t } from "../utils/I18n";
import { Button } from "./utility/Button";
import Divider from "./utility/Divider";
import { EndingDataViewer } from "./utility/EndingDataViewer";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

export default function Ending(props: {
  close: () => void;
  overrideEnding?: EndingStats;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const endingData = props.overrideEnding ?? getEndingData(state);
  if (endingData == null) {
    return <View />;
  }

  const ending = Endings.getById(endingData.endingId);

  return (
    <View style={styles.container}>
      <Text h1>{ending?.title}</Text>
      <View style={styles.explanation}>
        <Markdown style={styles.explanationText}>
          {ending?.description}
        </Markdown>
      </View>
      <View style={styles.dividerContainer}>
        <Divider />
      </View>
      <View style={styles.congratsContainer}>
        <Text h1>{t("Congratulations!")}</Text>
        <Text h2>{t("You have cleared Magic Research 2!")}</Text>
      </View>
      <View style={Styles.mb16}>
        <EndingDataViewer endingData={endingData} />
      </View>
      <View style={Styles.mt16}>
        <Button onPress={props.close}>{t("Close")}</Button>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  explanation: {
    marginTop: 16,
    flex: 1,
    alignItems: "stretch",
    width: "100%",
  },
  explanationText: {
    width: "100%",
  },
  dividerContainer: {
    marginTop: 32,
    marginBottom: 24,
  },
  congratsContainer: {
    alignItems: "center",
    marginBottom: 16,
  },
});
