import { ActionArea } from "../action/ActionAreas";
import { LevelUpListeners } from "../events/progression/LevelUp";
import { createHypotheticalState } from "../GameState";
import { getElementExp, grantElementExp } from "./Elements";
import { GameStartupListeners } from "./GameStartup";
import { getVisibleSpellsForElement, Spells, toggleEmpowered } from "./Spells";
import { clearVisibleSpellsCache } from "./VisibleSpellsCache";

export function loadSpellCommonLogic() {
  LevelUpListeners.register((state, element, oldExp) => {
    let lessExpState = createHypotheticalState(state);
    lessExpState = grantElementExp(
      element,
      -(getElementExp(state, element) - oldExp),
    )(lessExpState);
    const oldSpells = getVisibleSpellsForElement(
      lessExpState,
      element,
      ActionArea.SPELL_MENU,
    );
    const newSpells = getVisibleSpellsForElement(
      state,
      element,
      ActionArea.SPELL_MENU,
    );
    const oldEmpoweredSpells = oldSpells.filter((spell) =>
      spell.isEmpoweringPossible(lessExpState),
    );
    const newEmpoweredSpells = newSpells.filter(
      (spell) =>
        spell.isEmpoweringPossible(state) &&
        !oldEmpoweredSpells.includes(spell),
    );
    for (let spell of newEmpoweredSpells) {
      state = toggleEmpowered(state, spell);
    }
    return state;
  }, "spellAutoEmpowering");
  LevelUpListeners.register((state, element, oldExp) => {
    return clearVisibleSpellsCache(state);
  }, "spellByElementCacheClearer");
  GameStartupListeners.register((state) => {
    const spells = Spells.getAll();
    for (let spell of spells) {
      if (spell.isEmpoweringPossible(state)) {
        state = toggleEmpowered(state, spell);
      }
    }
    return state;
  }, "spellAutoEmpoweringOnStartup");
}
