import { t } from "../../../../../utils/I18n";
import { AttackTarget } from "../../../../exploration/AttackTarget";
import { CombatStat } from "../../../../exploration/CombatStats";
import { GameState } from "../../../../GameState";
import { Resource } from "../../../../Resources";
import { SpellElement } from "../../../../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../../../../transformation/Transformation";
import { EquipmentSlot } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class RingOfRegeneration extends EquippableItem {
  getId(): string {
    return "ringOfRegeneration";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Accessory;
  }
  getPicture() {
    return require("./ringOfRegeneration.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Ring of Regeneration");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A ring that greatly magnifies the wearer's innate healing power.",
    );
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 3500;
  }

  getEffectExtra(state: GameState, params: ItemParams): string {
    return t("Doubles innate :hp: regeneration");
  }
}

export const ringOfRegeneration = new RingOfRegeneration();

class TransmuteRingOfRegeneration extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return ringOfRegeneration;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Water: 23,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 500,
      },
      items: {
        elementalShardWater: 120,
        elementalShardEarth: 20,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Water;
  }
}

registerTransformation(
  [[AttackTarget.Player, CombatStat.HPRegen]],
  ringOfRegeneration.getId(),
  ringOfRegeneration.getName(ringOfRegeneration.getDefaultParams()),
  TransformationType.Multiplier,
  (state, params) => {
    if (ringOfRegeneration.isEquippedWithAnyParams(state)) {
      return 2.0;
    }
    return 1.0;
  },
);

export const transmuteRingOfRegeneration = new TransmuteRingOfRegeneration();
