import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { AttackTarget } from "../../exploration/AttackTarget";
import {
  clearCombatStatCache,
  CombatStat,
} from "../../exploration/CombatStats";
import { ExplorationStatus } from "../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../exploration/ExplorationUtils";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";
import { healthBoost } from "./HealthBoost";

class FirstAid extends Enhancement {
  getId(): string {
    return "firstAid";
  }
  getBaseName(): string {
    return t("First Aid");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_EXPLORATION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 450,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 5,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [healthBoost];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Develop first aid techniques which allow you to heal wounds much faster when not fighting.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("+50% :hp: recovery speed when outside of battle");
  }
}

export const firstAid = new FirstAid();

registerTransformation(
  [[CombatStat.HPRegen, AttackTarget.Player]],
  firstAid.getId(),
  firstAid.getBaseName(),
  TransformationType.Multiplier,
  (state) =>
    firstAid.isPurchased(state) &&
    getExplorationStatus(state) != ExplorationStatus.Combat
      ? 1.5
      : 1.0,
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == firstAid) {
    state = clearCombatStatCache(state, AttackTarget.Player);
  }
  return state;
}, firstAid.getId());
