import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import DraggableFlatList, {
  RenderItemParams,
  ScaleDecorator,
} from "react-native-draggable-flatlist";
import { IconButton, useTheme } from "react-native-paper";
import { StateContextValue } from "../../backend/GameState";
import { Spell } from "../../backend/spells/Spell";
import {
  getFavoriteSpells,
  removeFromFavoriteSpells,
} from "../../backend/spells/Spells";
import { t } from "../../utils/I18n";
import CategoryContainer from "../utility/CategoryContainer";
import { DragHandle } from "../utility/DragHandle";
import { SpellView } from "../utility/SpellView";
import Styles from "../utility/Styles";
import { useGameState } from "../utility/useGameState";

function FavoriteSpellsContainerInside(props: { state: StateContextValue }) {
  const favoriteSpells = getFavoriteSpells(props.state).filter(
    (spell) => spell != null,
  );
  if (favoriteSpells.length === 0) {
    return null;
  }
  const stateRef = useRef(props.state);
  stateRef.current = props.state;

  const theme = useTheme();

  const renderItem = useCallback(
    ({ item, drag, isActive }: RenderItemParams<Spell>) => {
      const spell = item;
      return (
        <ScaleDecorator>
          <View style={Styles.flexRowAlignCenter} key={spell.getId()}>
            <DragHandle drag={drag} />
            <SpellView
              key={spell.getId()}
              spell={spell}
              isMinimal={true}
              tooltipExtra="favorite"
            />
            <IconButton
              icon="close"
              accessibilityLabel={t("Remove from Favorites")}
              style={styles.deleteButton}
              hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
              onPress={() =>
                stateRef.current.apply((state) =>
                  removeFromFavoriteSpells(state, spell),
                )
              }
            />
          </View>
        </ScaleDecorator>
      );
    },
    [removeFromFavoriteSpells, theme],
  );
  const keyExtractor = useCallback((spell: Spell) => spell.getId(), []);
  return useMemo(
    () => (
      <View style={Styles.flex}>
        <CategoryContainer
          title={t("Favorites")}
          containerStyle={styles.categoryContainer}
          contentContainerStyle={Styles.flex}
        >
          <View style={[Styles.mt8, Styles.flex]}>
            <DraggableFlatList
              data={favoriteSpells}
              renderItem={renderItem}
              containerStyle={Styles.flex}
              keyExtractor={keyExtractor}
              onDragEnd={(params) => {
                stateRef.current.apply((state) => {
                  state.run.favoriteSpells = params.data.map((spell) =>
                    spell.getId(),
                  );
                  return state;
                });
              }}
            />
          </View>
        </CategoryContainer>
      </View>
    ),
    [
      favoriteSpells.map((spell) => spell.getId()).join(),
      renderItem,
      keyExtractor,
    ],
  );
}

export function FavoriteSpellsContainer(props: { isVisible?: boolean }) {
  const state = useGameState();

  return useMemo(
    () => <FavoriteSpellsContainerInside state={state} {...props} />,
    [props.isVisible !== false ? state : false],
  );
}

const styles = StyleSheet.create({
  deleteButton: {
    marginLeft: 8,
    width: 32,
    height: 32,
  },
  deleteButtonInner: {
    paddingHorizontal: 0,
    paddingVertical: 0,
    height: 32,
    width: 32,
    borderRadius: 16,
  },
  categoryContainer: {
    paddingTop: 8,
    flex: 1,
  },
});
