import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { AttackTarget } from "./AttackTarget";

export type CombatActionResult = {
  id: string;
  damage: number;
  isHit: boolean;
  isCrit: boolean;
  time: number;
  source: AttackTarget;
  target: AttackTarget;
  tags: string[];
};

export type CombatActionResultListener = (
  state: GameState,
  result: CombatActionResult,
) => GameState;

const CombatActionResultListeners = new DataStore<CombatActionResultListener>();

export { CombatActionResultListeners };

export function pushCombatActionResult(
  state: GameState,
  combatActionResult: CombatActionResult,
): GameState {
  state.run.exploration.actionResultQueue.push(combatActionResult);
  state.run.exploration.lastActionResultPushedTime = getSecondsPlayed(state);

  // Prevent super long action queues
  if (state.run.exploration.actionResultQueue.length > 20) {
    state.run.exploration.actionResultQueue =
      state.run.exploration.actionResultQueue.slice(-10);
  }

  CombatActionResultListeners.getAll().forEach((listener) => {
    state = listener(state, combatActionResult);
  });
  return state;
}
