import { registerRootComponent } from "expo";
import "intl-pluralrules";
import { useEffect, useState } from "react";
import "react-native-gesture-handler";
import "setimmediate";
import "./i18n";
import AppContents from "./src/AppContents";
import AudioManager from "./src/audio/AudioManager";
import { loadDemoGame } from "./src/backend/DemoLoader";
import { setTooltipComponentForMarkdown } from "./src/components/utility/Markdown";
import Tooltip from "./src/components/utility/tooltip/PaperTooltip";

function loadGame() {
  loadDemoGame();

  setTooltipComponentForMarkdown(Tooltip);
  AudioManager.load();
}

export default function App() {
  const [loading, setLoading] = useState(true);
  useEffect(() => {
    loadGame();
    setLoading(false);
  }, []);
  if (loading) {
    return null;
  }
  return <AppContents />;
}

registerRootComponent(App);
