import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { essenceCatalyst } from "../../buildings/catalysts/EssenceCatalyst";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";

class SuperheatedCatalysts extends Enhancement {
  getId(): string {
    return "superheatedCatalysts";
  }
  getBaseName(): string {
    return t("Superheated Catalysts");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_CATALYSTS;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 400,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 3,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Apply a special technique to Essence Catalysts to increase their effectivity.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("+15% effectivity of Essence Catalysts");
  }
}

const superheatedCatalysts = new SuperheatedCatalysts();

registerTransformation(
  [[essenceCatalyst.getId(), TransformationTags.CatalystEffectivity]],
  superheatedCatalysts.getId(),
  superheatedCatalysts.getBaseName(),
  TransformationType.Multiplier,
  (state) => (superheatedCatalysts.isPurchased(state) ? 1.15 : 1.0),
);

export { superheatedCatalysts };
