import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { ActivityIndicator } from "react-native-paper";
import {
  getCurrentFamiliarAction,
  getFamiliarActionProgress,
} from "../../backend/exploration/Exploration";
import {
  FamiliarCompanionStatusType,
  getCurrentCompanionData,
} from "../../backend/familiars/Familiar";
import { ProgressBar } from "../utility/ProgressBar";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { GameIcon } from "../utility/icons/GameIcon";
import { useGameState } from "../utility/useGameState";

export function FamiliarActionDisplay() {
  const state = useGameState();
  const currentFamiliarAction = getCurrentFamiliarAction(state);
  const ready = [
    FamiliarCompanionStatusType.Exploring,
    FamiliarCompanionStatusType.Combat,
  ].includes(getCurrentCompanionData(state).status.companionStatus);
  const progress = getFamiliarActionProgress(state);

  return useMemo(() => {
    if (!ready) {
      return (
        <View style={[styles.row, Styles.flexCenter]}>
          <ActivityIndicator size="small" />
        </View>
      );
    }
    if (!currentFamiliarAction) {
      return (
        <View style={styles.row}>
          <GameIcon icon="attack" style={styles.icon} />
          <View style={styles.progressBarContainer}>
            <Text style={styles.idleTextContainer}>-</Text>
            <ProgressBar progress={0} style={styles.progressBar} />
          </View>
        </View>
      );
    }
    return (
      <View style={styles.row}>
        <GameIcon icon="attack" style={styles.icon} />
        <View style={styles.progressBarContainer}>
          <Text numberOfLines={1} style={styles.markdownContainer}>
            {currentFamiliarAction.name}
          </Text>
          <ProgressBar progress={progress} style={styles.progressBar} />
        </View>
      </View>
    );
  }, [currentFamiliarAction?.name, progress, ready]);
}

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    width: 148,
  },
  icon: {
    marginRight: 4,
    height: 24,
    width: 24,
  },
  progressBarContainer: {
    flex: 1,
    marginTop: -5,
  },
  idleTextContainer: {
    height: 21,
  },
  markdownContainer: {
    height: 21,
  },
  progressBar: {
    width: 120,
  },
});
