import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  clearTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";
import { Wet } from "./Wet";

class Seared extends TemporaryEffect {
  getId(): string {
    return "seared";
  }
  getDisplayName(): string {
    return t("Seared");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("-30%:defense:");
  }
}

const seared = new Seared();

export { seared as Seared };

export function loadSeared() {
  TemporaryEffects.register(seared);

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.Defense, target]],
      "seared" + target,
      seared.getDisplayName(),
      TransformationType.Multiplier,
      (state, params) => {
        return hasTemporaryEffect(state, seared.getId(), target) ? 0.7 : 1.0;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    seared.getId(),
    CombatStat.Defense,
  );

  addGameStateListener("searedClearerIfWet", (state) => {
    for (let target of valuesOf(AttackTarget)) {
      if (
        hasTemporaryEffect(state, Wet.getId(), target) &&
        hasTemporaryEffect(state, seared.getId(), target)
      ) {
        return clearTemporaryEffect(seared.getId(), target);
      }
    }
  });
}
