import mem from "mem";
import memoize from "sonic-memoize";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { addGameStateListener } from "../../GameStateListeners";
import { ExplorationStatus } from "../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../exploration/ExplorationUtils";
import { hasTemporaryEffect } from "../../temporaryeffects/TemporaryEffects";
import {
  applyTransformations,
  explainTransformationsText,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { EquipmentSlot, getEquippedItems } from "../Equipment";
import { ItemAction, ItemParams } from "../Item";
import { Items } from "../Items";
import { EquippableItem } from "./EquippableItem";

const memoizedPrimary = mem(
  (
    enabled: boolean,
    active: boolean,
    id: string,
    index: number,
    params: ItemParams,
  ) => {
    return {
      id: "primaryPouch",
      description: t("Use"),
      isEnabled: enabled,
      isActive: active,
      transform: (state: GameState) => {
        // Re-check enabled
        const item = Items.getById(id) as PouchItem;
        const refreshedAction = item.getPouchItemAction(state, index, params);
        if (refreshedAction?.isEnabled) {
          state.run.exploration.pouchItemsUsesLeft[index] =
            (state.run.exploration.pouchItemsUsesLeft[index] ??
              item.getUsesPerCombat(state, params)) - 1;
          state = (Items.getById(id) as PouchItem).doPouchItemEffect(
            state,
            params,
          );
        }
        return state;
      },
    };
  },
  { cacheKey: JSON.stringify },
);

export abstract class PouchItem extends EquippableItem {
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Pouch;
  }

  abstract doPouchItemEffect(state: GameState, params: ItemParams): GameState;

  getPouchItemAction(
    state: GameState,
    index: number,
    params: ItemParams,
  ): ItemAction | undefined {
    const isStunned = hasTemporaryEffect(state, "stun");
    const active = this.isActive(state);
    const enabled =
      !isStunned &&
      getExplorationStatus(state) == ExplorationStatus.Combat &&
      !hasUsedUpPouchItemThisCombat(state, index);
    return memoizedPrimary(enabled, active, this.getId(), index, params);
  }

  // This function is called when a combat finishes
  doCleanup(state: GameState, params: ItemParams): GameState {
    return state;
  }

  shouldAutoUse(state: GameState, params: ItemParams): boolean {
    return true;
  }

  getUsesPerCombatBase(params: ItemParams): number {
    return 1;
  }

  getUsesPerCombatTags = memoize(() => [
    ...this.getTransformationTags(),
    TransformationTags.PouchItemUses,
  ]);

  getUsesPerCombat(state: GameState, params: ItemParams): number {
    return Math.floor(
      applyTransformations(
        this.getUsesPerCombatTags(),
        state,
        this.getUsesPerCombatBase(params),
        {
          itemId: this.getId(),
          params,
        },
      ),
    );
  }

  getUsesPerCombatExplanation(state: GameState, params: ItemParams): string {
    return explainTransformationsText(
      this.getUsesPerCombatTags(),
      state,
      this.getUsesPerCombatBase(params),
      { itemId: this.getId(), params },
    );
  }
}

export function hasUsedUpPouchItemThisCombat(
  state: GameState,
  index: number,
): boolean {
  const usesLeft = state.run.exploration.pouchItemsUsesLeft?.[index];
  return usesLeft <= 0;
}

export function getUsesLeftForPouchItem(
  state: GameState,
  index: number,
): number {
  const usesLeft = state.run.exploration.pouchItemsUsesLeft?.[index];
  if (usesLeft != null) {
    return usesLeft;
  }
  const occurrence = getEquippedItems(state, EquipmentSlot.Pouch)[index];
  if (occurrence == null) {
    return 0;
  }
  const id = occurrence.itemId;
  const params = occurrence.params;
  const item = Items.getById(id) as PouchItem;
  const uses = item.getUsesPerCombat(state, params);
  state.run.exploration.pouchItemsUsesLeft[index] = uses;
  return uses;
}

export function loadPouch() {
  addGameStateListener("pouchUser", (state) => {
    if (!state.global.autoUsePouch) {
      return;
    }
    state.temp.isInPouch = true;
    const pouchItems = getEquippedItems(state, EquipmentSlot.Pouch);
    let used = false;
    do {
      used = false;
      for (let index in pouchItems) {
        // Shortcut for perf
        if (
          getExplorationStatus(state) != ExplorationStatus.Combat ||
          hasUsedUpPouchItemThisCombat(state, Number.parseInt(index))
        ) {
          continue;
        }
        const itemOccurrence = pouchItems[index];
        const item = Items.getByIdNullable(itemOccurrence?.itemId) as PouchItem;
        if (!item) {
          continue;
        }
        const action = item.getPouchItemAction(
          state,
          Number.parseInt(index),
          itemOccurrence.params,
        );
        if (
          action.isEnabled &&
          item.shouldAutoUse(state, itemOccurrence.params)
        ) {
          state = action?.transform?.(state) ?? state;
        }
      }
    } while (used);
    state.temp.isInPouch = false;
  });
}
