import { t } from "../../../../utils/I18n";
import { getAccumulatorVariable } from "../../../AccumulatorVariables";
import { addGameStateListener } from "../../../GameStateListeners";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

const advertisingManual = new Storyline(
  "advertisingManual",
  t("Advertising Manual"),
  20,
  10,
  t("Sell a total of 1000 items or more"),
  t("+50%:coins: from selling items"),
  (state) => true,
);

export function loadAdvertisingManual() {
  Storylines.register(advertisingManual);

  const advertisingManualEventBuilder = buildEvent(
    "advertisingManual",
    t("(Storyline) Advertising Manual"),
    [EventTag.STORYLINE],
  ).setStorylineIds(advertisingManual.getId());

  advertisingManualEventBuilder.message(
    t(`After finishing a recent transaction, you're delighted - you managed to get a fair amount of :coins: for a simple item!
  
You walk back home, but you are interrupted in your tracks by a man with a huge backpack. "Excuse me," he says, "may I have a few moments of your attention?"`),
  );
  advertisingManualEventBuilder.message(
    t(`He introduces himself: "I am a traveling merchant. I buy and sell things for a living." Then he continues: "I couldn't help but note how... *simple*... that last sale was. You just went right up to your buyer, and... handed over your good for the posted price."
  
It feels like a reprimand: "That's definitely not how you should do things. When you are selling a product, any price you see posted is just *the bare minimum* you could manage to sell it for. You have gone through quite some effort just acquiring your item, and they are giving you *a misery* for it!"

You haven't had much experience as a merchant, and really didn't think much about it...`),
  );
  advertisingManualEventBuilder
    .message(
      t(`"I have something just for you. This *book*," he exclaims, while taking something out from his backpack and showing it to you. It is, indeed, a book: the cover reads "The Basics of Advertising".
  
"With this book, you'll be able to get much more value out of the things that you decide to sell - if you do a lot of this, it will *definitely* come in handy!"

It sounds like a good idea, until he says: "I'd be happy to sell it to you for *10K:coins:*. Which I'm sure you'll be able to make back in no time!" That price feels very *steep*. You really feel like this merchant is using that very technique on you. You're not sure of whether you should buy it or not...`),
    )
    .option(
      t("Buy the book (10K Coins)"),
      {
        transform: (state, params) =>
          grantResource(Resource.Coins, -10000)(state),
        isEnabled: (state, params) =>
          getResourceAmount(state, Resource.Coins) >= 10000,
      },
      "purchased",
    )
    .option("Maybe later")
    .end();
  advertisingManualEventBuilder
    .message(
      t(`You decide to give over the money. It was difficult to accumulate that much, but you are convinced this book could really be valuable. The merchant smiles while taking your :coins:. "Nice doing business with you," he says, then hands over the book. "Best of luck on your future sales!" He waves at you and walks away.
  
You decide to head home...`),
    )
    .tag("purchased")
    .option(t("Continue"), {
      transform: (state, params) => advertisingManual.complete(state),
    });
  advertisingManualEventBuilder.message(
    t(`From the comfort of your home, you finally decide to take out the book and read it so you can apply it right away. Then you feel heartbroken. It does indeed describe techniques about how to advertise, but... they seem *so complex and difficult to understand*! You begin to regret your purchase. Will you ever be able to master this?
  
For now, you decide to put it away. You don't feel like this is going to help you very much right now. But you think that perhaps someone in the future might be able to use it...

**You have completed the "Advertising Manual" Storyline! In future runs, you will gain +50%:coins: when selling items!**`),
  );

  const advertisingManualEvent = advertisingManualEventBuilder.build();

  addGameStateListener("advertisingManualTriggerer", (state) => {
    if (
      !advertisingManual.isCompleted(state) &&
      !advertisingManualEvent.hasTriggered(state) &&
      getAccumulatorVariable(state, "itemsSold") >= 1000
    ) {
      return triggerEvent(advertisingManualEvent);
    }
  });

  registerTransformation(
    [[TransformationTags.ItemSalePrice]],
    advertisingManual.getId(),
    advertisingManual.getName(),
    TransformationType.Multiplier,
    (state) => (advertisingManual.isBonusActive(state) ? 1.5 : 1.0),
  );
}

export { advertisingManual };
