import { valuesOf } from "../../../utils/CoreUtils";
import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { essenceStorage } from "../../buildings/storages/EssenceStorage";
import { GameState } from "../../GameState";
import { clearResourceCapCache, Resource } from "../../Resources";
import { RESOURCE_FOR_ELEMENT } from "../../spells/channeling/ChannelingResources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";
import { compressedEssence } from "./CompressedEssence";

class WideStorage extends Enhancement {
  getId(): string {
    return "wideStorage";
  }
  getBaseName(): string {
    return t("Wide Storage");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_STORAGE;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 6000,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 6,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [compressedEssence];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Improve your Essence Storages so that they have even more room to store Essence.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("3x maximum Essence from Essence Storages");
  }
}

const wideStorage = new WideStorage();

registerTransformation(
  valuesOf(RESOURCE_FOR_ELEMENT).map((resource) => [
    resource,
    TransformationTags.PerBuildingCap,
    essenceStorage.getId(),
  ]),
  wideStorage.getId(),
  wideStorage.getBaseName(),
  TransformationType.Multiplier,
  (state) => (wideStorage.isPurchased(state) ? 3.0 : 1.0),
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == wideStorage) {
    state = clearResourceCapCache(state);
  }
  return state;
}, wideStorage.getId());

export { wideStorage };
