import { t } from "../../../../utils/I18n";
import { isAutocastUnlocked } from "../../../autocast/Autocast";
import {
  getElementLevel,
  getPrimaryElementMaxLevelForRun,
  SpellElement,
} from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { LevelUpListeners } from "../../progression/LevelUp";

const simultaneousChanneling = new Storyline(
  "simultaneousChanneling",
  t("Simultaneous Channeling"),
  10,
  10,
  t("Reach Lv8 on your primary Element"),
  t('Unlock spell "Channel Four"'),
  (state) => true,
);

export function loadSimultaneousChanneling() {
  Storylines.register(simultaneousChanneling);

  const simultaneousChannelingEventBuilder = buildEvent(
    "simultaneousChanneling",
    t("(Storyline) Simultaneous Channeling"),
    [EventTag.STORYLINE],
  ).setStorylineIds(simultaneousChanneling.getId());

  simultaneousChannelingEventBuilder
    .message(
      t(`Your expertise with magic continues to increase. Once more, you think about Channeling - getting essences of your primary element is proving fruitful, but you begin to think that it is actually a lot of work to have to think about many elements at once...
    
You wonder if perhaps there could be a spell that could channel many elements at once... You think you will probably need even more expertise to figure this out.`),
    )
    .option(
      t(
        "Research a new spell (Requires Fire Lv7, Earth Lv7, Water Lv7, Air Lv7)",
      ),
      {
        transform: (state) => simultaneousChanneling.complete(state),
        isEnabled: (state) =>
          getElementLevel(state, SpellElement.Fire) >= 7 &&
          getElementLevel(state, SpellElement.Earth) >= 7 &&
          getElementLevel(state, SpellElement.Water) >= 7 &&
          getElementLevel(state, SpellElement.Air) >= 7,
      },
      "end",
    )
    .option(t("Maybe later"))
    .end();
  simultaneousChannelingEventBuilder
    .message(
      t(`You come up with a new idea! You prepare yourself and get ready to cast the spell, and then you do so... and you are startled as a sudden explosion happens in front of you! You hear giggles from behind you - your hired wizard has definitely taken notice of your failure.
    
It appears that isn't going to be enough. You are convinced it has something to do with your skills, but you make sure to write down your ideas in your diary, so that if anyone else sees it in the future they would be able to try it...

**You have completed the "Simultaneous Channeling" Storyline! In future runs, you will have a new spell available, to channel Fire, Earth, Water and Air at once: "Channel Four"!**`),
    )
    .tag("end");

  const simultaneousChannelingEvent =
    simultaneousChannelingEventBuilder.build();

  LevelUpListeners.register((state, element) => {
    if (!state.run.primaryElement) {
      return state;
    }
    if (
      !simultaneousChanneling.isCompleted(state) &&
      !simultaneousChannelingEvent.hasTriggered(state) &&
      getPrimaryElementMaxLevelForRun(state) >= 8 &&
      isAutocastUnlocked(state)
    ) {
      return triggerEvent(simultaneousChannelingEvent)(state);
    }
    return state;
  }, simultaneousChannelingEvent.getId());
}

export { simultaneousChanneling };
