import { DungeonFloor } from "../../exploration/dungeons/DungeonFloor";
import {
  addEnemyDeathListener,
  getCurrentDungeonFloor,
  getSuccessfulExplorationsForFloor,
} from "../../exploration/Exploration";
import { ExplorationStatus } from "../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../exploration/ExplorationUtils";
import { GameState } from "../../GameState";
import { DataStore } from "../../generic/DataStore";
import { Identifiable } from "../../generic/Identifiable";
import { triggerEvent } from "../Events";
import { GameEvent } from "../GameEvent";

export type ExplorationEvent = {
  event: GameEvent;
  getWeight: (state: GameState) => number;
  getParams?: (state: GameState) => any;
};

export abstract class ExplorationTriggerGroup implements Identifiable {
  explorationEvents: ExplorationEvent[] = [];

  abstract getId(): string;

  abstract getExplorationSuccessesBase(): number;

  abstract getDungeonFloors(): DungeonFloor[];

  abstract getHintMessageWhenPossible(state: GameState): string;
  abstract getHintMessageWhenNotPossible(state: GameState): string;

  getHintMessage(state: GameState): string {
    if (this.isDoneForThisRun(state)) {
      return this.getHintMessageWhenNotPossible(state);
    }
    return this.getHintMessageWhenPossible(state);
  }

  maxTriggersPerRun(): number | undefined {
    return undefined;
  }

  isDoneForThisRun(state: GameState): boolean {
    const maxTriggers = this.maxTriggersPerRun();
    if (maxTriggers === undefined) {
      return false;
    }

    const timesTriggered =
      state.run.explorationTriggers[this.getId()]?.timesTriggered ?? 0;
    if (timesTriggered >= maxTriggers) {
      return true;
    }

    return false;
  }

  addExplorationEvent(event: ExplorationEvent) {
    this.explorationEvents.push(event);
  }

  getExplorationAmountNeeded(state: GameState): number {
    if (state.run.explorationTriggers[this.getId()] == undefined) {
      state.run.explorationTriggers[this.getId()] = {
        timesTriggered: 0,
        next:
          this.getExplorationSuccessesBase() *
          (1 + 0.5 * (0.5 - Math.random())),
      };
    }
    return state.run.explorationTriggers[this.getId()].next;
  }

  getTotalExplorationSuccesses(state: GameState): number {
    return this.getDungeonFloors().reduce(
      (accumulated, floor) =>
        accumulated + getSuccessfulExplorationsForFloor(state, floor.getId()),
      0,
    );
  }

  checkAndTrigger(state: GameState): GameState {
    const totalExplorations = this.getTotalExplorationSuccesses(state);
    if (
      getExplorationStatus(state) != ExplorationStatus.None &&
      this.getDungeonFloors().includes(getCurrentDungeonFloor(state)) &&
      totalExplorations >= this.getExplorationAmountNeeded(state)
    ) {
      const totalWeight = this.explorationEvents.reduce(
        (sum, current) => sum + current.getWeight(state),
        0,
      );
      if (totalWeight <= 0) {
        return state;
      }

      let randomValue = Math.random() * totalWeight;
      for (let event of this.explorationEvents) {
        randomValue -= event.getWeight(state);
        if (randomValue < 0) {
          // We have selected an event and should trigger it
          const timesTriggered =
            state.run.explorationTriggers[this.getId()].timesTriggered + 1;
          state.run.explorationTriggers[this.getId()].timesTriggered =
            timesTriggered;
          state.run.explorationTriggers[this.getId()].next =
            totalExplorations +
            Math.pow(timesTriggered + 1, 1.9) *
              this.getExplorationSuccessesBase() *
              (1 + 0.3 * (0.5 - Math.random()));
          state = triggerEvent(
            event.event,
            event?.getParams?.(state) ?? {},
          )(state);
          return state;
        }
      }
    }
    return state;
  }
}

export const ExplorationTriggerGroups =
  new DataStore<ExplorationTriggerGroup>();

addEnemyDeathListener("explorationTriggerGroups", (state) => {
  ExplorationTriggerGroups.getAll().forEach((group) => {
    state = group.checkAndTrigger(state);
  });
  return state;
});
