import {
  DungeonEventGroup,
  getOrCreateDungeonEventGroup,
} from "../../events/DungeonEventGroup";
import { GameState } from "../../GameState";
import { Identifiable } from "../../generic/Identifiable";
import { DungeonFloor } from "./DungeonFloor";

export abstract class Dungeon implements Identifiable {
  abstract getId(): string;
  abstract getName(): string;
  abstract getDungeonFloors(): DungeonFloor[];
  abstract isUnlocked(state: GameState): boolean;

  getDungeonEventGroup(): DungeonEventGroup {
    return getOrCreateDungeonEventGroup(this.getId());
  }

  hasTriggeredEvent(state: GameState): boolean {
    const triggered =
      this.getDungeonEventGroup().hasFinishedTriggeringThisRun(state);
    return triggered;
  }
}
