import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Cherry } from "../../../items/equipment/pouch/Cherry";
import Wildgrass from "../../../items/material/Wildgrass";
import { standardEnemyAttackEffect } from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class FireBat extends Enemy {
  getId(): string {
    return "fireBat";
  }

  getName(): string {
    return t("Fire Bat");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 60,
      attack: 27,
      defense: 8,
      attackDelay: 4.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./fireBat.png");
  }

  getLevel(): number {
    return 2;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 17;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 3;
  }

  getNextAction(state: GameState): BattlerAction {
    if (Math.random() < 0.25) {
      return {
        name: t("Burning Bite"),
        delay: this.getAttackDelay(state) * 2.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 1.5);
          return state;
        },
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 1,
        chance: 0.2,
      },
      {
        itemId: Cherry.getId(),
        amount: 1,
        chance: 0.02,
      },
    ];
  }
}

export default new FireBat();
