export const STUB_OPTION: EventMessageOptionStub = {};

type EventMessageOptionWithVisibility = {
  option: Omit<EventMessageOption, "isEnabled">;
  isVisible?: (state: GameState, params?: any) => boolean;
  isEnabled?: (state: GameState, params?: any) => boolean;
};

function replaceWithParams(text: string, params: any) {
  if (!params) {
    return text;
  }
  Object.keys(params)
    .map((key) => [
      new RegExp("/" + key + "/", "g"),
      isNaN(params[key]) || params[key] === ""
        ? params[key]
        : formatNumber(params[key]),
    ])
    .forEach((replacement) => {
      text = text.replace(replacement[0], replacement[1]);
    });
  return text;
}

export class EventMessageImpl implements EventMessage {
  constructor(
    private id: string,
    private title: string,
    private description: (state: GameState) => string,
    private options: EventMessageOptionWithVisibility[],
  ) {
    registerEventMessage(this);
  }

  getId(): string {
    return this.id;
  }
  getTitle(params: any): string {
    return replaceWithParams(this.title, params);
  }
  getDescription(state: GameState, params: any): string {
    return replaceWithParams(this.description(state), params);
  }
  getAllowedOptions(state: GameState, params: any): EventMessageOption[] {
    return this.options
      .filter(
        (option) => option.isVisible == null || option.isVisible(state, params),
      )
      .map((option) => ({
        ...option.option,
        description: replaceWithParams(option.option.description, params),
        isEnabled: option.isEnabled == null || option.isEnabled(state, params),
      }));
  }
  isTerminal(): boolean {
    return this.options.every(
      (option) => !option.option.nextMessage && !option.option.transform,
    );
  }
  isActionable(state: GameState, params: any): boolean {
    return this.getAllowedOptions(state, params).some(
      (option) => (option.nextMessage || option.transform) && option.isEnabled,
    );
  }
}

type EventMessageOptionStub = {
  description?: string;
  nextMessageId?: string;
  transform?: (state: GameState, params: any) => GameState;
  isVisible?: (state: GameState, params: any) => boolean;
  isEnabled?: (state: GameState, params: any) => boolean;
};

class EventMessageBuilderDeluxe {
  id: string;
  description: (state: GameState) => string = () => "";
  options: EventMessageOptionStub[] = [];
  isEnd: boolean = false;
  built: boolean;

  constructor(id: string) {
    this.id = id;
  }

  tag(id: string) {
    this.id = id;
    return this;
  }

  setDescription(description: (state: GameState) => string) {
    this.description = description;
    return this;
  }

  setSimpleDescription(description: string) {
    this.setDescription((state: GameState) => description);
    return this;
  }

  option(
    description: string,
    paramsOrNextMessageId:
      | {
          transform?: (state: GameState, params: any) => GameState;
          isVisible?: (state: GameState, params: any) => boolean;
          isEnabled?: (state: GameState, params: any) => boolean;
        }
      | string = {},
    nextMessageId?: string,
  ) {
    if (typeof paramsOrNextMessageId === "string") {
      nextMessageId = paramsOrNextMessageId;
      paramsOrNextMessageId = {};
    }

    this.options.push({
      description,
      nextMessageId,
      transform: paramsOrNextMessageId?.transform,
      isVisible: paramsOrNextMessageId?.isVisible,
      isEnabled: paramsOrNextMessageId?.isEnabled,
    });
    return this;
  }

  getOptions() {
    return this.options.length > 0 ? this.options : [STUB_OPTION];
  }

  end() {
    this.isEnd = true;
  }
}

class SimpleGameEvent implements GameEvent {
  triggerString: string;

  constructor(
    private id: string,
    private message: EventMessage,
    private tags: EventTag[],
    private onTriggerTransform?: EventTransform,
    private storylineIds?: string[],
  ) {
    registerGameEvent(this);
    this.triggerString = "event_triggered_" + this.id;
  }

  onTrigger(): EventTransform {
    return (state, params) => {
      state = setWorldFlag(this.triggerString)(state);
      state = setFlag(this.triggerString)(state);
      state = this.onTriggerTransform
        ? this.onTriggerTransform(state, params)
        : state;
      return state;
    };
  }

  hasTriggered(state: GameState): boolean {
    return hasFlag(state, this.triggerString);
  }

  hasEverTriggered(state: GameState): boolean {
    return hasWorldFlag(state, this.triggerString);
  }

  getId(): string {
    return this.id;
  }

  getMainEventMessage(): EventMessage {
    return this.message;
  }

  getTags(): EventTag[] {
    return this.tags;
  }

  getStorylines(): Storyline[] {
    return (this.storylineIds ?? []).map((id) =>
      Storylines.getByIdNullable(id),
    );
  }
}

function createGameEvent(
  id: string,
  message: EventMessage,
  tags: EventTag[],
  onTrigger?: EventTransform,
  storylineIds?: string[],
): GameEvent {
  return new SimpleGameEvent(id, message, tags, onTrigger, storylineIds);
}

class EventBuilder {
  constructor(
    private id: string,
    private title: string,
    private tags: EventTag[],
  ) {}

  messages: EventMessageBuilderDeluxe[] = [];

  private lastMessageId: number = 0;
  private onTrigger?: EventTransform;
  private storylineIds?: string[];

  message(description: string, tag?: string): EventMessageBuilderDeluxe {
    const builder = new EventMessageBuilderDeluxe(tag ?? this.newMessageId());
    builder.setSimpleDescription(description);
    this.messages.push(builder);
    return builder;
  }

  newMessageId(): string {
    return (this.lastMessageId++).toString();
  }

  setOnTrigger(onTrigger: EventTransform) {
    this.onTrigger = onTrigger;
    return this;
  }

  setStorylineIds(storylineIds: string | string[]) {
    this.storylineIds =
      typeof storylineIds === "string" ? [storylineIds] : storylineIds;
    return this;
  }

  build(): GameEvent {
    // Start from the first message
    const message = this.buildRecursive(0);
    if (message == null) {
      throw new Error("Events should always have at least one message");
    }
    // Make sure all messages are built!
    for (let i in this.messages) {
      if (!this.messages[i].built) {
        this.buildRecursive(Number.parseInt(i));
      }
    }
    return createGameEvent(
      this.id,
      message,
      this.tags,
      this.onTrigger,
      this.storylineIds,
    );
  }

  private findMessageIndexWithId(idToSearch: string | undefined): number {
    return this.messages.findIndex(
      (messageBuilder) => messageBuilder.id === idToSearch,
    );
  }

  private buildRecursive(index: number): EventMessage | undefined {
    const messageBuilder = this.messages?.[index];
    if (!(messageBuilder instanceof EventMessageBuilderDeluxe)) {
      return undefined;
    }
    const options = messageBuilder.getOptions().map((optionStub) => {
      let nextMessageId = optionStub?.nextMessageId;

      if (nextMessageId == null && !messageBuilder.isEnd) {
        nextMessageId = this.messages?.[index + 1]?.id;
      }

      const nextMessageIndex = this.findMessageIndexWithId(nextMessageId);

      let message: EventMessage | undefined = undefined;

      if (nextMessageIndex >= 0) {
        message = this.buildRecursive(nextMessageIndex);
      }
      return {
        option: {
          description:
            optionStub.description ??
            (nextMessageId != null ? t("Continue") : t("Close")),
          transform: optionStub.transform,
          nextMessage: message,
        },
        isVisible: optionStub.isVisible,
        isEnabled: optionStub.isEnabled,
      };
    });
    messageBuilder.built = true;
    return new EventMessageImpl(
      this.id + messageBuilder.id,
      this.title,
      messageBuilder.description,
      options,
    );
  }
}

export function buildEvent(id: string, title: string, tags: EventTag[]) {
  return new EventBuilder(id, title, tags);
}
