import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { Spell } from "../../spells/Spell";
import {
  AutocastCondition,
  AutocastConditionParameter,
} from "../AutocastCondition";

class AlwaysAutocastCondition extends AutocastCondition {
  getId(): string {
    return "Always";
  }
  getDisplayName(): string {
    return t("Always");
  }
  getParameters(): AutocastConditionParameter[] {
    return [];
  }
  getDisplayExplanation(state: GameState, values: string[]): string {
    return t("Always");
  }
  isConditionMet(
    state: GameState,
    spell: Spell | undefined,
    values: string[],
  ): boolean {
    return true;
  }
  validate(state: GameState, values: string[]): Record<number, string> {
    return {};
  }
}

export default new AlwaysAutocastCondition();
