import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { TransformationType } from "../transformation/Transformation";
import { TemporaryEffect } from "./TemporaryEffect";
import { TemporaryEffectData, TemporaryEffects } from "./TemporaryEffects";

class Weakened extends TemporaryEffect {
  getId(): string {
    return "weakened";
  }
  getDisplayName(): string {
    return t("Weakened");
  }
  getIcon() {
    return require("./weakened.png");
  }
  isBeneficial(): boolean {
    return false;
  }
  isCommonBuffEnabled(): { stat: CombatStat; type: TransformationType }[] {
    return [{ stat: CombatStat.Attack, type: TransformationType.Multiplier }];
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("-{{amount}}%:attack:", {
      amount: formatNumber(
        100 * (1.0 - temporaryEffectData.params.commonBuff.AttackMultiplier),
      ),
    });
  }
}

export const weakened = new Weakened();

export function loadWeakened() {
  TemporaryEffects.register(weakened);
}
