import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { StyleProp, View, ViewStyle } from "react-native";
import { TextInput, useTheme } from "react-native-paper";
import { GameState } from "../../backend/GameState";
import {
  loadBase64Data,
  makeCompatible,
  saveDataUnsafe,
} from "../../backend/SaveData";
import { compareVersions } from "../../utils/CoreUtils";
import {
  LOAD_FROM_FILE_SUPPORTED,
  loadSaveFromFile,
} from "../../utils/FilesystemSave";
import { t } from "../../utils/I18n";
import {
  getCurrentVersion,
  getCurrentVersionTimestamp,
} from "../../utils/Versioning";
import { Button } from "./Button";
import Styles from "./Styles";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

export default function LoadGameContainer(props: {
  style?: StyleProp<ViewStyle>;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  const [loadMessage, setLoadMessage] = useState<string | null>("");
  const [loadSaveContents, setLoadSaveContents] = useState("");

  const loadSaveFromDeserializedData = useCallback(
    async (deserializedData: GameState) => {
      try {
        // Only in the demo version, fail loading saves coming from the full version.
        const isDemo = false;
        if (isDemo && deserializedData.global.isFullVersionSave) {
          setLoadMessage(
            t(
              "Failed to load save. This save data belongs to the full version of the game, and cannot be used in the demo version.",
            ),
          );
          return;
        }

        // Only in the full version, fail loading saves coming from the full version
        // if they come from a future version.
        const currentAppVersionTimestamp =
          getCurrentVersionTimestamp() || +Infinity;
        const currentAppVersion = getCurrentVersion() ?? "99.99.99";
        const versionCompare = compareVersions(
          currentAppVersion,
          deserializedData.global.appVersionString,
          2,
        );
        if (
          !isDemo &&
          deserializedData.global.isFullVersionSave &&
          (currentAppVersionTimestamp <
            deserializedData.global.appVersionTimestamp -
              1000 * 60 * 60 * 24 * 30 ||
            versionCompare < 0)
        ) {
          setLoadMessage(
            t(
              "Failed to load save. This save data was made with a more recent version of the game. Please update your game and try again. If no update is available, please try restarting the game or your device several times and loading the save again.",
            ),
          );
          return;
        }
        const compatibleData = makeCompatible(deserializedData);
        const skipAheadTime = Math.max(
          Math.min(
            60 * 60 * 48,
            (Date.now() - compatibleData.run.lastProcessedTime) / 1000,
          ),
          0,
        );
        compatibleData.run.skipAheadTime = skipAheadTime;
        stateRef.current.overwrite((_oldState) => {
          return compatibleData;
        });
        await saveDataUnsafe(compatibleData);
        setLoadMessage(t("Load successful!"));
      } catch (e) {
        setLoadMessage(
          t(
            "Failed to load save because of an unexpected error! Is the data valid?",
          ),
        );
      }
    },
    [],
  );
  const importSavefile = useCallback(async () => {
    if (loadSaveContents === "") {
      setLoadMessage(
        t("Please paste your save in the text field above this message first!"),
      );
      return;
    }
    try {
      const deserializedData = await loadBase64Data(loadSaveContents);
      if (deserializedData != null) {
        await loadSaveFromDeserializedData(deserializedData);
      } else {
        setLoadMessage(
          t("Failed to load save. Make sure you have copied the full text!"),
        );
      }
    } catch (e) {
      setLoadMessage(
        t("Failed to load save. Make sure you have copied the full text!"),
      );
    }
  }, [loadSaveContents, loadSaveFromDeserializedData]);
  useEffect(() => setLoadMessage(""), [loadSaveContents]);

  const theme = useTheme();

  const showByFile = LOAD_FROM_FILE_SUPPORTED;

  const importSavefileFromFile = useCallback(async () => {
    try {
      const result = await loadSaveFromFile();
      if (result.result === "error") {
        setLoadMessage(
          t(
            "There was an error reading from the file. Is it a valid Magic Research save? {{error}}",
            { error: result.error },
          ),
        );
      }
      if (result.result === "success") {
        await loadSaveFromDeserializedData(result.data);
      }
    } catch (e) {
      setLoadMessage(
        t(
          "There was an error reading from the file. Is it a valid Magic Research save?",
        ),
      );
    }
  }, [loadSaveFromDeserializedData]);

  return useMemo(
    () => (
      <View style={props.style}>
        <TextInput
          multiline={true}
          onChangeText={setLoadSaveContents}
          value={loadSaveContents}
          mode="outlined"
          label={t("Save text") ?? "Save text"}
          style={{ maxHeight: 120 }}
        />
        {loadMessage != "" && (
          <View style={Styles.mt8}>
            <Text style={{ color: theme.colors.error }}>{loadMessage}</Text>
          </View>
        )}
        {loadSaveContents != "" || !showByFile ? (
          <Button style={Styles.mt16} onPress={importSavefile}>
            {t("Import savefile")}
          </Button>
        ) : (
          <Button style={Styles.mt16} onPress={importSavefileFromFile}>
            {t("Load from file")}
          </Button>
        )}
      </View>
    ),
    [
      setLoadSaveContents,
      loadSaveContents,
      loadMessage,
      importSavefile,
      importSavefileFromFile,
    ],
  );
}
