import React, { useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import {
  EquipmentSlot,
  getEquippedExtraSlotItem,
  getEquippedItem,
  getMaxEquippedItemsPerSlot,
  getUnequipAction,
} from "../../backend/items/Equipment";
import { Items } from "../../backend/items/Items";
import { EquippableItem } from "../../backend/items/equipment/EquippableItem";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";
import { ItemInfo } from "./ItemInfo";
import { ItemTile } from "./ItemTile";

function EquippedPouchSlot(props: { index: number }) {
  const state = useGameState();
  const index = props.index;
  const equippedItem = getEquippedExtraSlotItem(
    state,
    EquipmentSlot.Pouch,
    index,
  );
  const actualEquippedItem = getEquippedItem(state, EquipmentSlot.Pouch, index);
  const unequipAction = getUnequipAction(state, EquipmentSlot.Pouch, index);

  const stateRef = useRef(state);
  stateRef.current = state;

  const item = equippedItem && Items.getById(equippedItem.itemId);
  if (item && !(item instanceof EquippableItem)) {
    throw new Error("Only equippable items should be equipped");
  }

  const showAsDisabled = equippedItem != actualEquippedItem;

  return useMemo(() => {
    if (!equippedItem) {
      return (
        <View key={index}>
          <ItemTile title={t("Empty")} />
          <Button
            compact={true}
            style={[styles.button, { height: 20 }]}
            labelStyle={{
              marginHorizontal: 0,
              flex: 1,
              marginVertical: 0,
              height: 20,
            }}
            contentStyle={{
              marginHorizontal: 0,
              height: 20,
            }}
            disabled={true}
            onPress={() =>
              unequipAction.transform &&
              stateRef.current.apply(unequipAction.transform)
            }
          >
            {unequipAction.description}
          </Button>
        </View>
      );
    }
    return (
      <View key={index}>
        <ItemTile
          icon={(item as EquippableItem).getId()}
          quality={equippedItem?.params?.itemQuality}
          disabled={showAsDisabled}
          title={(item as EquippableItem).getName(equippedItem.params)}
          tooltip={
            <ItemInfo
              showTile={false}
              itemStack={{ itemOccurrence: equippedItem, amount: 1 }}
            />
          }
          tooltipIdSuffix="pouch"
        />
        <Button
          compact={true}
          style={[styles.button, { height: 20 }]}
          labelStyle={{
            marginHorizontal: 0,
            flex: 1,
            marginVertical: 0,
            height: 20,
          }}
          contentStyle={{
            marginHorizontal: 0,
            height: 20,
          }}
          disabled={!unequipAction.isEnabled}
          onPress={() =>
            unequipAction.transform &&
            stateRef.current.apply(unequipAction.transform)
          }
        >
          {unequipAction.description}
        </Button>
      </View>
    );
  }, [
    JSON.stringify(equippedItem),
    showAsDisabled,
    index,
    unequipAction.description,
    unequipAction.isEnabled,
    unequipAction.transform,
    stateRef,
    item?.getName(equippedItem?.params || {}),
  ]);
}

export default function EquippedPouch(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const capacity = getMaxEquippedItemsPerSlot(state, EquipmentSlot.Pouch);
  const slotComponents = useMemo(() => {
    let slotComponents = [];
    for (let i = 0; i < capacity; i++) {
      slotComponents.push(<EquippedPouchSlot key={i} index={i} />);
    }
    return slotComponents;
  }, [capacity]);

  return useMemo(
    () =>
      capacity == 0 ? (
        <View />
      ) : (
        <View style={Styles.flexRowAlignCenter}>
          <View>
            <Text h4>{t("Pouch:")}</Text>
          </View>
          <View
            style={[
              Styles.ml16,
              Styles.flexRowAlignCenter,
              { flex: 1, gap: 4, flexWrap: "wrap" },
            ]}
          >
            {slotComponents}
          </View>
        </View>
      ),
    [slotComponents, capacity],
  );
}

const styles = StyleSheet.create({
  button: {
    height: 20,
  },
});
