import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Resource } from "../../../Resources";
import { SpellElement } from "../../../spells/Elements";
import { Item, ItemParams } from "../../Item";
import { ItemTagEnum } from "../../ItemTagEnum";
import { TransmutationSpell } from "../TransmutationSpell";

class Cotton extends Item {
  getId(): string {
    return "cotton";
  }

  getTags(): ItemTagEnum[] {
    return [ItemTagEnum.Material];
  }

  getPicture() {
    return require("./cotton.png");
  }

  getBaseName(params: ItemParams): string {
    return t("Cotton");
  }

  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A thread of cotton. Strong and durable, useful for making many kinds of clothing.",
    );
  }

  getEffect(state: GameState, params: ItemParams): string | undefined {
    return undefined;
  }

  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 80;
  }
}

export const cotton = new Cotton();

class TransmuteCotton extends TransmutationSpell {
  getItem(): Item {
    return cotton;
  }

  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Air: 22,
    };
  }

  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 600,
        AirEssence: 18000,
      },
      items: {},
    };
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }
}

export const transmuteCotton = new TransmuteCotton();
