import { MaterialIcons } from "@expo/vector-icons";
import { Platform } from "expo-modules-core";
import React from "react";
import { StyleProp, StyleSheet } from "react-native";
import { useTheme } from "react-native-paper";
import RNPicker from "react-native-picker-select";
import { t } from "../../utils/I18n";
import { useAuxOptions } from "./AuxOptionsContext";

function DropDownIcon() {
  const theme = useTheme();
  return (
    <MaterialIcons
      name="arrow-drop-down"
      size={24}
      color={theme.colors.onPrimary}
    />
  );
}

export type PickerData = { key: string; label: string; value: string };

export function Picker(props: {
  selectedValue: string;
  onValueChange: (itemValue: string, itemIndex: number) => void;
  data: PickerData[];
  disablePlaceholder?: boolean;
  maxLength?: number;
  style?: StyleProp<any>;
}) {
  const { style, selectedValue, ...rest } = props;
  const theme = useTheme();
  const auxOptions = useAuxOptions();
  return (
    <RNPicker
      {...rest}
      style={{
        inputAndroidContainer: {
          backgroundColor: theme.colors.primary,
          padding: 8,
          paddingVertical: auxOptions.compactLayout ? 0 : 8,
          paddingRight: 32,
          borderRadius: 4,
        },
        inputAndroid: StyleSheet.flatten([
          {
            color: theme.colors.onPrimary,
          },
          style,
        ]),
        iconContainer: {
          top: auxOptions.compactLayout ? 2 : 10,
          right: 4,
          width: Platform.OS === "web" ? 0 : 24,
          display: Platform.OS === "web" ? "none" : "flex",
        },
        viewContainer: StyleSheet.flatten([
          Platform.OS === "web"
            ? {
                alignSelf: "center",
              }
            : undefined,
          style,
        ]),
        placeholder: {
          color: theme.colors.onPrimary,
        },
      }}
      placeholder={props.disablePlaceholder ? {} : { label: t("Select...") }}
      fixAndroidTouchableBug={true}
      // @ts-expect-error Icon is not properly typed
      Icon={DropDownIcon}
      value={selectedValue}
      useNativeAndroidPickerStyle={false}
      dropdownIconColor={theme.colors.onBackground}
      dropdownIconRippleColor={theme.colors.onBackground}
      textInputProps={props.maxLength && { maxLength: props.maxLength }}
      items={props.data}
    ></RNPicker>
  );
}
