import { MaterialIcons } from "@expo/vector-icons";
import React, { useCallback, useMemo, useRef } from "react";
import {
  Platform,
  ScrollView,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
} from "react-native";
import DraggableFlatList, {
  DragEndParams,
  RenderItemParams,
  ScaleDecorator,
} from "react-native-draggable-flatlist";
import { useTheme } from "react-native-paper";
import {
  QuickbarAction,
  QuickbarActionImpl,
  getQuickbarActions,
  removeFromQuickbar,
} from "../../backend/Quickbar";
import { t } from "../../utils/I18n";
import { ActiveMark } from "../utility/ActiveMark";
import { Button } from "../utility/Button";
import { IconButton } from "../utility/IconButton";
import { KeyboardListener } from "../utility/KeyboardListener";
import { getPrimaryButtonTextColor } from "../utility/Miscellaneous";
import Styles from "../utility/Styles";

import { getThemeByElementAndDarkMode } from "../../Themes";
import { Spell } from "../../backend/spells/Spell";
import { formatNumber } from "../../utils/FormattingUtils";
import { TemporaryEffectsViewer } from "../utility/TemporaryEffectsViewer";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";

function QuickbarIndexSelector(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const currentQuickbar = state.world.currentQuickbar;
  const disableBackArrow = currentQuickbar <= 0;
  const disableForwardArrow = currentQuickbar >= 3;
  const slotNumber = currentQuickbar + 1;

  const goBack = useCallback(() => {
    stateRef.current.apply((state) => {
      state.world.currentQuickbar = state.world.currentQuickbar - 1;
      return state;
    });
  }, []);

  const goForward = useCallback(() => {
    stateRef.current.apply((state) => {
      state.world.currentQuickbar = state.world.currentQuickbar + 1;
      return state;
    });
  }, []);

  const theme = useTheme();

  const backButton = useMemo(
    () => (
      <Button
        disabled={disableBackArrow}
        accessibilityLabel={t("Previous Quickbar")}
        passthroughStyle={styles.quickbarSelectorButton}
        ButtonComponent={IconButton}
        size={24}
        icon={() => (
          <MaterialIcons
            name="arrow-back"
            size={24}
            color={
              disableBackArrow
                ? theme.colors.onSurfaceDisabled
                : theme.colors.onPrimary
            }
          />
        )}
        disableDisabledRenderingOptimization={true}
        onPress={goBack}
      />
    ),
    [disableBackArrow, goBack],
  );
  const forwardButton = useMemo(
    () => (
      <Button
        disabled={disableForwardArrow}
        accessibilityLabel={t("Next Quickbar")}
        passthroughStyle={styles.quickbarSelectorButton}
        ButtonComponent={IconButton}
        size={24}
        icon={() => (
          <MaterialIcons
            name="arrow-forward"
            size={24}
            color={
              disableForwardArrow
                ? theme.colors.onSurfaceDisabled
                : theme.colors.onPrimary
            }
          />
        )}
        disableDisabledRenderingOptimization={true}
        onPress={goForward}
      />
    ),
    [disableForwardArrow, goForward],
  );

  return useMemo(
    () => (
      <View
        style={[
          styles.row,
          {
            alignSelf: "stretch",
            justifyContent: "center",
          },
        ]}
      >
        {backButton}
        <View style={styles.barText}>
          <View>
            <Text>{t("Bar {{slot}}", { slot: formatNumber(slotNumber) })}</Text>
          </View>
          {Platform.OS === "web" && (
            <View>
              <Text style={{ color: theme.colors.tertiary }} numberOfLines={1}>
                {t("[Ctrl+1~4]")}
              </Text>
            </View>
          )}
        </View>
        {forwardButton}
      </View>
    ),
    [backButton, slotNumber, forwardButton],
  );
}

function QuickbarActionContainer(props: {
  quickbarAction: QuickbarAction;
  hotkey: string | undefined;
  drag: () => void;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const quickbarAction = props.quickbarAction;
  const name = quickbarAction.getDisplayName(state);
  const transform = quickbarAction.getTransform(state);
  const data = quickbarAction.toQuickbarData();
  const enabled = quickbarAction.isEnabled(state);
  const active = quickbarAction.isActive(state);

  const theme = useTheme();
  const elementTheme =
    quickbarAction instanceof QuickbarActionImpl &&
    state.options.colorSpellButtonsByElement
      ? getThemeByElementAndDarkMode(
          (quickbarAction.getAction() as Spell).getElement(),
          theme.dark,
        )
      : theme;

  const title = useMemo(() => {
    if (props.hotkey) {
      return (
        <Text
          style={[
            styles.smallButtonTitle,
            {
              color: getPrimaryButtonTextColor(elementTheme, !enabled),
            },
            Styles.flex,
          ]}
          numberOfLines={2}
        >
          <Text
            style={
              enabled
                ? { color: elementTheme.colors.onTertiary }
                : { color: getPrimaryButtonTextColor(elementTheme, !enabled) }
            }
          >
            [{props.hotkey}]
          </Text>{" "}
          {name}
        </Text>
      );
    }
    return (
      <Text
        style={[
          styles.smallButtonTitle,
          {
            color: getPrimaryButtonTextColor(elementTheme, !enabled),
          },
          Styles.flex,
        ]}
        numberOfLines={2}
      >
        {name}
      </Text>
    );
  }, [props.hotkey, name, elementTheme, enabled]);

  const onPress = useCallback(
    () => stateRef.current.apply(transform),
    [transform],
  );

  return useMemo(
    () => (
      <ScaleDecorator>
        <View style={[Styles.flex, Styles.flexRowAlignCenter, Styles.mv4]}>
          <View
            importantForAccessibility="no-hide-descendants"
            accessibilityElementsHidden={true}
            style={styles.dragHandle}
          >
            <TouchableWithoutFeedback
              onPressIn={props.drag}
              hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
              importantForAccessibility="no-hide-descendants"
              accessibilityElementsHidden={true}
            >
              <MaterialIcons
                name="drag-indicator"
                size={24}
                color={theme.colors.onSecondaryContainer}
              />
            </TouchableWithoutFeedback>
          </View>
          <View style={Styles.flex}>
            <Button
              icon={
                active
                  ? () => (
                      <ActiveMark
                        disabled={!enabled}
                        style={{ marginRight: -8 }}
                      />
                    )
                  : undefined
              }
              onPress={onPress}
              disabled={!enabled}
              compact={true}
              style={Styles.flex}
              labelStyle={{
                marginHorizontal: 16,
                flex: 1,
                marginVertical: 0,
                height: 40,
              }}
              contentStyle={{ marginHorizontal: 0, flex: 1, height: 40 }}
              TextComponent={View}
              enableAutoRepress={true}
              disableDisabledRenderingOptimization={true}
              numberOfLines={2}
              theme={elementTheme}
            >
              <View style={[Styles.flex, Styles.flexRowAlignCenter]}>
                <Text numberOfLines={2}>{title}</Text>
              </View>
            </Button>
          </View>
          <Button
            accessibilityLabel={t("Remove from Quickbar")}
            // importantForAccessibility="no-hide-descendants"
            // accessibilityElementsHidden={true}
            passthroughStyle={styles.deleteButton}
            ButtonComponent={IconButton}
            hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
            onPress={() =>
              stateRef.current.apply((state) => {
                return removeFromQuickbar(state, data);
              })
            }
            icon="close"
            disableDisabledRenderingOptimization={true}
          />
        </View>
      </ScaleDecorator>
    ),
    [
      name,
      stateRef,
      transform,
      enabled,
      active,
      removeFromQuickbar,
      props.hotkey,
      elementTheme,
      props.drag,
    ],
  );
}

export function QuickbarContainer() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const quickbar = getQuickbarActions(state);

  const renderItem = useCallback(
    ({ item, getIndex, drag, isActive }: RenderItemParams<QuickbarAction>) => (
      <QuickbarActionContainer
        hotkey={
          Platform.OS === "web"
            ? (((getIndex() ?? 0) + 1) % 10).toString()
            : undefined
        }
        drag={drag}
        quickbarAction={item}
      />
    ),
    [],
  );
  const keyExtractor = useCallback(
    (quickbarAction: QuickbarAction) =>
      JSON.stringify(quickbarAction.toQuickbarData()),
    [],
  );
  const quickbarKeypressCallback = useCallback(
    (ev: KeyboardEvent) => {
      if (ev.ctrlKey) {
        let slot = -1;
        switch (ev.key) {
          case "1":
            slot = 0;
            break;
          case "2":
            slot = 1;
            break;
          case "3":
            slot = 2;
            break;
          case "4":
            slot = 3;
            break;
        }
        if (slot > -1) {
          stateRef.current.apply((state) => {
            state.world.currentQuickbar = slot;
            return state;
          });
        }
        return;
      }
      let slot = -1;
      switch (ev.key) {
        case "1":
          slot = 0;
          break;
        case "2":
          slot = 1;
          break;
        case "3":
          slot = 2;
          break;
        case "4":
          slot = 3;
          break;
        case "5":
          slot = 4;
          break;
        case "6":
          slot = 5;
          break;
        case "7":
          slot = 6;
          break;
        case "8":
          slot = 7;
          break;
        case "9":
          slot = 8;
          break;
        case "0":
          slot = 9;
          break;
      }

      if (slot >= 0) {
        const quickbarAction = quickbar?.[slot];
        if (quickbarAction != null) {
          const transform = quickbarAction.getTransform(stateRef.current);
          const enabled = quickbarAction.isEnabled(stateRef.current);
          if (enabled) {
            stateRef.current.apply(transform);
          }
        }
      }
    },
    [JSON.stringify(quickbar)],
  );
  const onDragEnd = useCallback((params: DragEndParams<QuickbarAction>) => {
    stateRef.current.apply((state) => {
      state.world.quickbars[state.world.currentQuickbar] = params.data.map(
        (quickbarAction) => quickbarAction.toQuickbarData(),
      );
      return state;
    });
  }, []);
  return useMemo(
    () => (
      <View style={styles.mainContainer}>
        <KeyboardListener callback={quickbarKeypressCallback} />
        <View style={styles.actionsContainer}>
          {Platform.select({
            web: (
              <ScrollView
                style={styles.actionsScroll}
                nestedScrollEnabled={true}
              >
                {quickbar.length > 0 ? (
                  <DraggableFlatList
                    renderItem={renderItem}
                    data={quickbar}
                    keyExtractor={keyExtractor}
                    onDragEnd={onDragEnd}
                    nestedScrollEnabled={true}
                  />
                ) : (
                  <Text style={styles.emptyText}>
                    {t(
                      'Your Quickbar is empty. Add actions to the Quickbar through the "Spells" menu at the bottom!',
                    )}
                  </Text>
                )}
              </ScrollView>
            ),
            default:
              quickbar.length > 0 ? (
                <DraggableFlatList
                  renderItem={renderItem}
                  data={quickbar}
                  keyExtractor={keyExtractor}
                  onDragEnd={onDragEnd}
                  nestedScrollEnabled={true}
                />
              ) : (
                <Text style={styles.emptyText}>
                  {t(
                    'Your Quickbar is empty. Add actions to the Quickbar through the "Spells" menu at the bottom!',
                  )}
                </Text>
              ),
          })}
        </View>
        <View style={styles.infoContainer}>
          <View
            style={styles.accessibilityHidden}
            accessible={true}
            accessibilityLabel={t("Select Quickbar slot")}
            accessibilityRole="header"
            accessibilityElementsHidden={false}
          />
          <QuickbarIndexSelector />
          <ScrollView style={styles.infoContainer} nestedScrollEnabled={true}>
            <TemporaryEffectsViewer
              accessibilityHeader={t("Player Buffs")}
              maxElements={5}
            />
          </ScrollView>
        </View>
      </View>
    ),
    [
      renderItem,
      keyExtractor,
      onDragEnd,
      JSON.stringify(quickbar),
      quickbarKeypressCallback,
    ],
  );
}

const styles = StyleSheet.create({
  emptyText: { fontWeight: "bold", fontStyle: "italic" },
  row: { flexDirection: "row", alignItems: "center", marginVertical: 4 },
  mainContainer: {
    flexDirection: "row",
    alignItems: "stretch",
    height: "100%",
  },
  actionsContainer: {
    flex: 1.5,
    marginRight: 8,
  },
  infoContainer: { flex: 1 },
  actionsScroll: { flex: 1 },
  mainButton: {
    flex: 1,
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  deleteButton: {
    marginLeft: 8,
    paddingHorizontal: 0,
    paddingVertical: 0,
    height: 24,
    width: 24,
    borderRadius: 16,
  },
  quickbarSelectorButton: {
    height: 24,
    width: 24,
    borderRadius: 12,
    marginHorizontal: 4,
    paddingHorizontal: 0,
    paddingVertical: 0,
  },
  barText: {
    marginHorizontal: 4,
    alignItems: "center",
  },
  dragHandle: {
    cursor: "grab",
    marginRight: 8,
  },
  accessibilityHidden: {
    width: 1,
    height: 1,
    position: "absolute",
    left: 0,
    top: 0,
  },
});
