import { useMemo } from "react";
import { StyleSheet, TouchableOpacity, View } from "react-native";
import { useTheme } from "react-native-paper";
import { getThemeByElementAndDarkMode } from "../../Themes";
import { SpellElement, translateElement } from "../../backend/spells/Elements";
import { GameIcon } from "../utility/icons/GameIcon";

type SelectorItemProps = {
  element: SpellElement;
  selected: boolean;
  onPress: () => void;
  dividerColor?: string;
};

function SpellElementSelectorItem(props: SelectorItemProps) {
  const { element, selected, onPress } = { ...props };
  const theme = useTheme();
  const themeForElement = getThemeByElementAndDarkMode(element, theme.dark);
  return useMemo(
    () => (
      <TouchableOpacity
        onPress={onPress}
        accessible={true}
        accessibilityLabel={translateElement(element)}
        accessibilityRole="tab"
      >
        <View
          style={[
            styles.itemContainerStyle,
            { borderColor: props.dividerColor ?? theme.colors.outlineVariant },
            selected
              ? { backgroundColor: themeForElement.colors.primaryContainer }
              : null,
          ]}
        >
          <GameIcon icon={element.toLowerCase()} />
        </View>
      </TouchableOpacity>
    ),
    [onPress, element, theme, selected, props.dividerColor],
  );
}

export type Props = {
  onPress: (element: SpellElement) => void;
  elements: SpellElement[];
  selected: SpellElement;
  dividerColor?: string;
  style: any;
};

export function SpellElementSelector(props: Props) {
  return useMemo(
    () => (
      <View style={[styles.container, props.style]}>
        {props.elements.map((element) => (
          <SpellElementSelectorItem
            key={element}
            onPress={() => props.onPress(element)}
            element={element}
            selected={props.selected == element}
            dividerColor={props.dividerColor}
          />
        ))}
      </View>
    ),
    [
      props.style,
      props.elements.join(""),
      props.selected,
      props.onPress,
      props.dividerColor,
    ],
  );
}

const styles = StyleSheet.create({
  itemContainerStyle: {
    width: 34,
    height: 34,
    borderWidth: 1,
  },
  container: {
    flexDirection: "row",
    flexWrap: "wrap",
    gap: 2,
    alignItems: "center",
  },
});
