import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { clearLandCache, getTotalLand } from "../../buildings/Buildings";
import { GameState } from "../../GameState";
import { clearResourceCapCache, Resource } from "../../Resources";
import { Storylines } from "../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";

class FurnitureOrganization extends Enhancement {
  getId(): string {
    return "furnitureOrganization";
  }
  getBaseName(): string {
    return t("Furniture Organization");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_LAND;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 500,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 3,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Sort furniture in a neater way at your home to improve the amount of space available.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("+10% (+{{absoluteAmount}}):land:", {
      absoluteAmount: formatNumber(getTotalLand(state) * 0.1),
    });
  }
  isVisible(state: GameState): boolean {
    return (
      !Storylines.getByIdNullable("blessedLand")?.isBonusActive?.(state) &&
      super.isVisible(state)
    );
  }
}

const furnitureOrganization = new FurnitureOrganization();

registerTransformation(
  [[TransformationTags.Land, TransformationTags.Cap]],
  furnitureOrganization.getId(),
  furnitureOrganization.getBaseName(),
  TransformationType.Multiplier,
  (state) => (furnitureOrganization.isPurchased(state) ? 1.1 : 1.0),
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == furnitureOrganization) {
    state = clearResourceCapCache(state);
    state = clearLandCache(state);
  }
  return state;
}, furnitureOrganization.getId());

export { furnitureOrganization };
