import { t } from "../../../../utils/I18n";
import { Flag } from "../../../Flag";
import { hasFlag } from "../../../Flags";
import { GameState } from "../../../GameState";
import { SpellElement } from "../../../spells/Elements";
import { Enemy } from "../../enemies/Enemy";
import FlyingRock from "../../enemies/training/FlyingRock";
import RockGolem from "../../enemies/training/RockGolem";
import Slime from "../../enemies/training/Slime";
import {
  DungeonFloor,
  encounterEnemy,
  ExplorationPossibility,
} from "../DungeonFloor";

const EXPLORATION_POSSIBILITIES = [
  {
    weight: 100,
    transforms: encounterEnemy(Slime),
  },
  {
    weight: 150,
    transforms: encounterEnemy(FlyingRock),
  },
];
class TrainingTowerFire extends DungeonFloor {
  getId(): string {
    return "trainingTowerFire";
  }
  getFloorName(): string {
    return t("Tower of Fire Training");
  }
  getBoss(state: GameState): Enemy {
    return RockGolem;
  }
  getBreedingLevel(): number {
    return 1;
  }
  isMainStoryDungeonFloor(): boolean {
    return true;
  }
  getExplorationRequiredTimeSecBase(): number {
    return 4.0;
  }
  _getExplorationSuccessesForBossBase(): number {
    return 30;
  }
  getBaseExplorationPossibilities(state: GameState): ExplorationPossibility[] {
    return EXPLORATION_POSSIBILITIES;
  }
  isUnlocked(state: GameState): boolean {
    return (
      ![SpellElement.Air, SpellElement.Earth, SpellElement.Water].includes(
        state.run.primaryElement,
      ) || hasFlag(state, Flag.RestOfTrainingUnlocked)
    );
  }
}

export default new TrainingTowerFire();
