import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import Wildgrass from "../../../items/material/Wildgrass";
import { HasteTemporaryEffect } from "../../../spells/air/Haste";
import { SpellElement } from "../../../spells/Elements";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  getCurrentEnemyHPFraction,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class Windglider extends Enemy {
  getId(): string {
    return "windglider";
  }

  getName(): string {
    return t("Windglider");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 700,
      attack: 16,
      defense: 5,
      attackDelay: 1.0,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./windglider.png");
  }

  getLevel(): number {
    return 4;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 60;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 4;
  }

  getNextAction(state: GameState): BattlerAction {
    if (
      !hasTemporaryEffect(
        state,
        HasteTemporaryEffect.getId(),
        AttackTarget.Enemy,
      ) &&
      getCurrentEnemyHPFraction(state) < 0.5
    ) {
      return {
        name: t("Haste"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: grantTemporaryEffect(HasteTemporaryEffect.getId(), 15, {
          target: AttackTarget.Enemy,
          params: { commonBuff: { AttackDelayMultiplier: 0.85 } },
        }),
      };
    }
    if (Math.random() < 0.3) {
      return {
        name: t("Air Blast"),
        delay: this.getAttackDelay(state) * 1.3,
        transform: (state) =>
          standardEnemyAttackEffect(state, this.getAttack(state) * 1.3, {
            tags: [SpellElement.Air],
            cause: t("Air Blast"),
          }),
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 2,
        chance: 1.0,
      },
    ];
  }

  isBoss(): boolean {
    return true;
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "trainingTowerEarth");
    return state;
  }

  getTips(): string {
    return t(
      "The guardian of the Tower of Earth Training is an avian embodying Air. How does that make any sense?! Well... it attacks very fast, and will attack even faster after it uses Haste. You will need high :defense: for this one! Make sure to also have at least one way to heal! It is not too resilient, so just try to take down its health.",
    );
  }
}

export default new Windglider();
