import React, { useContext, useEffect, useMemo, useRef } from "react";
import {
  Animated,
  Platform,
  Text as RNText,
  StyleSheet,
  View,
} from "react-native";
import { useTheme } from "react-native-paper";
import {
  AttackTarget,
  translateAttackTarget,
} from "../../backend/exploration/AttackTarget";
import { CombatActionResult } from "../../backend/exploration/ExplorationActionResult";
import { getSecondsPlayed } from "../../backend/timetick/TotalTimePlayed";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ProgressBar } from "../utility/ProgressBar";
import { VisibilityContext } from "../utility/VisibilityContext";
import { GameIcon } from "../utility/icons/GameIcon";
import { useGameState } from "../utility/useGameState";

function CombatActionResultDisplay(props: {
  action: CombatActionResult;
  delay: number;
}) {
  const [auxText, auxTextStyle] = useMemo(() => {
    let auxText = null,
      auxTextStyle = null;
    if (props.action != undefined) {
      if (!props.action.isHit) {
        auxText = t("Miss!");
      } else {
        auxText =
          (props.action.damage > 0 ? "-" : "") +
          formatNumber(Math.abs(props.action.damage), {
            showDecimals: false,
          }) +
          (props.action.isCrit ? "!" : "");
        auxTextStyle = props.action.isCrit
          ? styles.auxTextCritical
          : props.action.damage >= 0
          ? styles.auxTextDamage
          : styles.auxTextHealing;
      }
    }
    return [auxText, auxTextStyle];
  }, [props.action]);

  const damageAnim = useRef(new Animated.Value(0)).current;
  const opacityAnim = useRef(new Animated.Value(0)).current;
  useEffect(() => {
    damageAnim.setValue(0);
    Animated.sequence([
      Animated.delay(props.delay),
      Animated.spring(damageAnim, {
        toValue: -16,
        useNativeDriver: true,
        bounciness: 0,
      }),
    ]).start();
  }, [props.action?.id]);
  useEffect(() => {
    Animated.sequence([
      Animated.delay(props.delay),
      Animated.timing(opacityAnim, {
        toValue: 1,
        useNativeDriver: true,
        duration: 0,
      }),
      Animated.timing(opacityAnim, {
        toValue: 0,
        useNativeDriver: true,
        duration: 800,
      }),
    ]).start();
  }, [props.action?.id]);

  const theme = useTheme();

  return useMemo(
    () =>
      auxText ? (
        <Animated.View
          style={[
            styles.auxTextContainer,
            { transform: [{ translateY: damageAnim }], opacity: opacityAnim },
          ]}
        >
          <RNText
            style={[
              styles.auxText,
              { color: theme.colors.onBackground },
              auxTextStyle,
            ]}
          >
            {auxText}
          </RNText>
        </Animated.View>
      ) : (
        <View />
      ),
    [damageAnim, opacityAnim, theme, auxText, auxTextStyle],
  );
}

function HPBarNonMemo(props: {
  empty: boolean;
  current: number;
  max: number;
  actionTarget: AttackTarget;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const visible = useContext(VisibilityContext);
  const mountTime = useRef(getSecondsPlayed(state));

  useEffect(() => {
    mountTime.current = getSecondsPlayed(stateRef.current);
  }, [visible]);

  const actions = useMemo(
    () =>
      !state.options.hideDamageEffects
        ? state.run.exploration.actionResultQueue.filter(
            (result) =>
              result != null &&
              result.target === props.actionTarget &&
              result.time > mountTime.current,
          )
        : [],
    [
      state.options.hideDamageEffects,
      props.actionTarget,
      state.run.exploration.lastActionResultPushedTime,
    ],
  );
  const actionKeys = useMemo(
    () => actions.map((action) => action.id).join(),
    [actions],
  );
  const actionResults = useMemo(() => {
    let lastTime = 0,
      delay = 0;
    const displays = [];
    for (let actionIdx in actions) {
      const action = actions[actionIdx];
      if (action.time * 1000 < lastTime + 300) {
        delay += lastTime + 300 - action.time * 1000;
      } else {
        delay = 0;
      }
      displays.push(
        <CombatActionResultDisplay
          key={action.id}
          action={action}
          delay={delay}
        />,
      );
      lastTime = action.time * 1000;
    }
    return displays;
  }, [actionKeys]);

  const theme = useTheme();

  const style = useMemo(
    () => [
      styles.bar,
      // @ts-ignore The hpBarBackground is not part of react-native-elements
      { backgroundColor: theme.colors.hpBarBackground },
    ],
    [theme],
  );

  const progressBar = useMemo(
    () => (
      <ProgressBar
        progress={props.current / props.max}
        // @ts-ignore The hpBarForeground is not part of react-native-elements
        color={theme.colors.hpBarForeground}
        style={style}
      />
    ),
    [props.current / props.max, theme, style],
  );

  const progressBarText = useMemo(
    () =>
      !props.empty && (
        <RNText style={styles.overlaidText}>
          {formatNumber(Math.ceil(props.current))}/
          {formatNumber(Math.ceil(props.max))}
        </RNText>
      ),
    [props.empty, formatNumber(Math.ceil(props.current)), props.max],
  );

  const accessibilityLabel = useMemo(
    () =>
      t("{{target}} HP: {{current}}/{{max}} {{shortcut}}", {
        target: translateAttackTarget(props.actionTarget),
        current: formatNumber(Math.ceil(props.current), {
          showDecimals: false,
        }),
        cap: formatNumber(Math.ceil(props.max), { showDecimals: false }),
        shortcut: Platform.OS === "web" ? t(" (Control + Shift + Z)") : "",
      }),
    [
      props.actionTarget,
      formatNumber(Math.ceil(props.current), {
        showDecimals: false,
      }),
      props.max,
    ],
  );

  return useMemo(
    () => (
      <View
        style={[styles.row, styles.container]}
        accessibilityLabel={accessibilityLabel}
        accessible={true}
        accessibilityRole={Platform.OS !== "web" ? "header" : undefined}
      >
        {/* @ts-expect-error react-native-web supports this */}
        <GameIcon icon="hp" style={styles.icon} accessibilityHidden={true} />
        {/* @ts-expect-error react-native-web supports this */}
        <View style={styles.progressBarContainer} accessibilityHidden={true}>
          {progressBar}
          {progressBarText}
          {actionResults}
        </View>
      </View>
    ),
    [
      accessibilityLabel,
      progressBar,
      progressBarText,
      props.empty,
      props.actionTarget,
      actionResults,
    ],
  );
}

const HPBar = React.memo(HPBarNonMemo);

export { HPBar };

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginTop: -4,
    marginRight: 4,
    height: 24,
    width: 24,
  },
  overlaidText: {
    color: "#fff",
    fontWeight: "bold",
    position: "absolute",
    top: Platform.select({
      default: 0,
      android: -1,
    }),
    width: "100%",
    textAlign: "center",
  },
  auxTextContainer: {
    left: 48,
    position: "absolute",
    width: "100%",
  },
  auxText: {
    width: "100%",
    textAlign: "center",
    fontWeight: "bold",
  },
  auxTextCritical: {
    color: "#dd0",
  },
  auxTextDamage: {
    color: "#f00",
  },
  auxTextHealing: {
    color: "#0f0",
  },
  progressBarContainer: {
    flex: 1,
  },
  container: {
    height: 24,
  },
  bar: {
    height: 20,
    borderRadius: 10,
  },
});
