import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import {
  hasEverHadTemporaryEffect,
  TemporaryEffects,
} from "../../../temporaryeffects/TemporaryEffects";
import {
  AutocastConditionParameterDisplay,
  AutocastConditionParameterOption,
  AutocastConditionParameterType,
} from "../../AutocastConditionParameterType";

export class TemporaryEffectAutocastConditionParameterType extends AutocastConditionParameterType {
  getDisplay(): AutocastConditionParameterDisplay {
    return AutocastConditionParameterDisplay.PICKER;
  }

  getOptions(state: GameState): AutocastConditionParameterOption[] {
    return TemporaryEffects.getAll()
      .filter((effect) => hasEverHadTemporaryEffect(state, effect.getId()))
      .map((effect) => ({
        label: effect.getDisplayName(),
        value: effect.getId(),
      }));
  }

  validate(state: GameState, value: string): string | undefined {
    if (TemporaryEffects.getByIdNullable(value) == null) {
      return t("You must select a buff / debuff");
    }
    return;
  }
}
