import React, { useMemo, useState } from "react";
import { StyleSheet, View } from "react-native";
import { SegmentedButtons } from "react-native-paper";
import { t } from "../utils/I18n";
import Bestiary from "./compendium/Bestiary";
import ItemCompendium from "./compendium/ItemCompendium";
import { GameStateFreezer } from "./utility/GameStateFreezer";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";

export default function Compendium() {
  const [selectedTab, setSelectedTab] = useState("item");
  const buttons = useMemo(
    () => [
      { value: "item", label: t("Items") },
      { value: "bestiary", label: t("Bestiary") },
    ],
    [],
  );
  const buttonSelectors = (
    <SegmentedButtons
      value={selectedTab}
      onValueChange={setSelectedTab}
      buttons={buttons}
    />
  );

  return (
    <View>
      <View style={styles.headerContainer}>
        <Text h1>{t("Compendium")}</Text>
      </View>

      {buttonSelectors}

      <View style={Styles.mt16}>
        <View style={selectedTab != "item" && { display: "none" }}>
          <GameStateFreezer isVisible={selectedTab == "item"}>
            <ItemCompendium />
          </GameStateFreezer>
        </View>
        <View style={selectedTab != "bestiary" && { display: "none" }}>
          <GameStateFreezer isVisible={selectedTab == "bestiary"}>
            <Bestiary />
          </GameStateFreezer>
        </View>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  headerContainer: {
    marginBottom: 16,
  },
  captionText: {
    flex: 1,
  },
});
