import { keysOf } from "../../utils/CoreUtils";
import { Action, DoActionArgs } from "../action/Action";
import { ActionArea, ActionSubcategory } from "../action/ActionAreas";
import { hasEverHadFlag, hasFlag, setFlag } from "../Flags";
import { GameState } from "../GameState";
import {
  monstiumPaper,
  transmuteMonstiumPaper,
} from "../items/transmute/materials/MonstiumPaper";
import { Resource } from "../Resources";
import { EnhancementPurchaseListeners } from "./EnhancementPurchaseListeners";

export abstract class Enhancement extends Action {
  abstract getBaseName(): string;
  abstract getSubcategory(): ActionSubcategory;
  abstract getBaseResourceCost(): Partial<Record<Resource, number>>;
  abstract getItemCost(): Record<string, number>;

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { ENHANCEMENT: [this.getSubcategory()] };
  }

  getDisplayName(state: GameState): string {
    return this.getBaseName();
  }

  isVisible(state: GameState): boolean {
    return (
      !this.isPurchased(state) &&
      this.getRequiredEnhancements().every((enhancement) =>
        enhancement.isPurchased(state),
      )
    );
  }

  getRequiredEnhancements(): Enhancement[] {
    return [];
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    // TODO: transformations
    return {
      resources: this.getBaseResourceCost(),
      items: this.getItemCost(),
    };
  }

  isCapEnough(state: GameState): boolean {
    return (
      super.isCapEnough(state) &&
      (keysOf(this.getItemCost()).includes(monstiumPaper.getId())
        ? transmuteMonstiumPaper.isVisible(state)
        : true)
    );
  }

  isPurchased(state: GameState): boolean {
    return hasFlag(state, "enhancement-" + this.getId());
  }

  hasEverBeenPurchased(state: GameState): boolean {
    return hasEverHadFlag(state, "enhancement-" + this.getId());
  }

  setPurchased(state: GameState): GameState {
    state = setFlag("enhancement-" + this.getId())(state);
    EnhancementPurchaseListeners.getAll().forEach((transform) => {
      state = transform(state, this);
    });
    return state;
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }

    state = this.payCost(state);

    return this.setPurchased(state);
  }
}
