import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat, getCachedCombatStat } from "../exploration/CombatStats";
import {
  BattlerAction,
  BattlerStats,
  getCombatStatFromBattlerStat,
} from "../exploration/enemies/Enemy";
import { standardFamiliarAttackEffect } from "../exploration/Exploration";
import { GameState } from "../GameState";
import {
  getLevelByExpAndExponent,
  getTotalExpRequiredForLevelByExponent,
} from "../spells/Elements";
import {
  applyTransformations,
  applyTransformationsCached,
  applyTransformationsPermaCached,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import {
  FamiliarCompanionStatusType,
  FamiliarData,
  FamiliarStatusType,
  getCurrentCompanionId,
  getFamiliarData,
} from "./Familiar";

const FAMILIAR_EXP_REQUIREMENT_MULTIPLIER = 0.015;

export const FAMILIAR_LEVEL_CAP_BASE = 90;

const cachedStats = {
  maxHP: [AttackTarget.Familiar, CombatStat.MaxHP, "maxHP"],
  attack: [AttackTarget.Familiar, CombatStat.Attack, "attack"],
  attackDelay: [AttackTarget.Familiar, CombatStat.AttackDelay, "attackDelay"],
  defense: [AttackTarget.Familiar, CombatStat.Defense, "defense"],
  accuracy: [AttackTarget.Familiar, CombatStat.Accuracy, "accuracy"],
  dodge: [AttackTarget.Familiar, CombatStat.Dodge, "dodge"],
  crit: [AttackTarget.Familiar, CombatStat.Crit, "crit"],
};

const FAMILIAR_MAX_LEVEL_TAGS = [TransformationTags.MaxFamiliarLevel];

export abstract class FamiliarClass {
  abstract getId(): string;
  abstract getPicture(state: GameState): any;
  abstract getName(): string;
  abstract getBaseStats(): BattlerStats;
  abstract getPerLevelStatMultiplier(): BattlerStats;

  getNextAction(state: GameState, familiarId: string): BattlerAction {
    return {
      name: t("Attack"),
      delay: this.getStats(state, familiarId).attackDelay,
      transform: (state) =>
        standardFamiliarAttackEffect(
          state,
          this.getStats(state, familiarId).attack,
        ),
    };
  }

  getNextActionForCompanion(state: GameState): BattlerAction {
    return this.getNextAction(state, getCurrentCompanionId(state));
  }

  getStats(
    state: GameState,
    familiarId: string,
    onlyStat?: CombatStat,
  ): BattlerStats {
    const level = this.getLevel(state, familiarId);
    const baseStats = this.getBaseStats();
    const multipliers = this.getPerLevelStatMultiplier();

    const stats = {};

    for (let stat in baseStats) {
      if (onlyStat == null || onlyStat == getCombatStatFromBattlerStat(stat)) {
        stats[stat] = getCachedCombatStat(
          state,
          AttackTarget.Familiar,
          getCombatStatFromBattlerStat(stat),
          (state) => {
            const statFromLevel =
              baseStats[stat] * Math.pow(multipliers[stat], level - 1);
            return applyTransformations(
              cachedStats[stat],
              state,
              statFromLevel,
            );
          },
          familiarId,
          level,
        );
      }
    }

    return stats as BattlerStats;
  }

  getMaxHP(state: GameState, familiarId: string): number {
    const level = this.getLevel(state, familiarId);
    const baseStats = this.getBaseStats();
    const multipliers = this.getPerLevelStatMultiplier();

    return getCachedCombatStat(
      state,
      AttackTarget.Familiar,
      CombatStat.MaxHP,
      (state) => {
        const statFromLevel =
          baseStats.maxHP * Math.pow(multipliers.maxHP, level - 1);
        return applyTransformationsCached(
          cachedStats.maxHP,
          state,
          statFromLevel,
        );
      },
      familiarId,
      level,
    );
  }

  getStatsForCompanion(state: GameState, onlyStat?: CombatStat): BattlerStats {
    return this.getStats(state, getCurrentCompanionId(state), onlyStat);
  }

  getLevel(state: GameState, familiarId: string): number {
    const levelEstimate = state.temp.familiarLevelCache.get(familiarId);
    const level = getLevelByExpAndExponent(
      getFamiliarData(state, familiarId).exp /
        FAMILIAR_EXP_REQUIREMENT_MULTIPLIER,
      1.38,
      this.getMaxLevel(state, familiarId),
      levelEstimate,
    );
    if (level != levelEstimate) {
      state.temp.familiarLevelCache.set(familiarId, level);
    }
    return level;
  }

  getMaxLevel(state: GameState, familiarId: string): number {
    return applyTransformationsPermaCached(
      FAMILIAR_MAX_LEVEL_TAGS,
      state,
      FAMILIAR_LEVEL_CAP_BASE,
    );
  }

  getExpForNextLevel(state: GameState, familiarId: string): number {
    return (
      getTotalExpRequiredForLevelByExponent(
        this.getLevel(state, familiarId) + 1,
        1.38,
        this.getMaxLevel(state, familiarId),
      ) * FAMILIAR_EXP_REQUIREMENT_MULTIPLIER
    );
  }

  create(state: GameState): GameState {
    const newId = Math.random().toString();
    const data: FamiliarData = {
      id: newId,
      familiarClassId: this.getId(),
      exp:
        getTotalExpRequiredForLevelByExponent(5, 1.38, 10) *
        FAMILIAR_EXP_REQUIREMENT_MULTIPLIER,
      status: {
        status: FamiliarStatusType.Idle,
        exploringDungeonFloorId: undefined,
        exploringNextRandomRoll: Math.random(),
        autoExploring: false,
        triggerFamiliarDeath: false,
        currentHp: this.getBaseStats().maxHP,
        companionStatus: FamiliarCompanionStatusType.Exploring,
        actionProgress: 0,
      },
      messageLog: [],
    };
    state.run.familiars[newId] = data;
    data.status.currentHp = this.getStats(state, newId).maxHP;
    return state;
  }
}
