import { t } from "../../../../utils/I18n";
import { FLOOR_FOR_ELEMENT } from "../../../exploration/dungeons/training/TrainingTowers";
import { getCurrentDungeonFloor } from "../../../exploration/Exploration";
import { ExplorationStatus } from "../../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../../exploration/ExplorationUtils";
import { GameState } from "../../../GameState";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import {
  getElementLevel,
  SpellElement,
  translateElement,
} from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { registerRandomEventTrigger } from "../../autorandom/RandomEventTrigger";
import { buildEvent } from "../../EventMessageBuilder";
import { EventTag } from "../../GameEvent";

function elementToUse(state: GameState): SpellElement {
  return [
    SpellElement.Fire,
    SpellElement.Earth,
    SpellElement.Water,
    SpellElement.Air,
  ].includes(state.run.primaryElement)
    ? state.run.primaryElement
    : SpellElement.Fire;
}

const studentContestStoryline = new Storyline(
  "studentContest",
  t("The Student Contest"),
  120,
  10,
  (state) =>
    t("Explore the Tower of {{element}} Training", {
      element: translateElement(elementToUse(state)),
    }),
  t("+5 extra Wizards"),
  (_state) => true,
);

export function loadStudentContest() {
  Storylines.register(studentContestStoryline);

  const studentContestEventBuilder = buildEvent(
    "studentContest",
    t("(Storyline) Student Contest"),
    [EventTag.STORYLINE],
  ).setStorylineIds(studentContestStoryline.getId());

  studentContestEventBuilder.message(
    t(`Climbing the tower, as you climb yet another flight of stairs, you notice this floor is different. It is a large, flat floor, with no individual walls. But most interestingly, it is full of *students* - all gathered around a figure who is elevated, on a sort of stage. You hear the figure exclaim:
    
"Show me that you can *really* handle /element/ for a big prize!" From the voice, it sounds like an enthusiastic young man. Is he also a student as well?`),
  );
  studentContestEventBuilder
    .message(
      t(
        `You head closer, and you start noticing that the students seem to be trying quite hard to cast /element/ spells - and yet you can't help but notice how much they are struggling.
      
You feel like showing them how to really do it. You prepare your energy and ready yourself up for a spell.`,
      ),
    )
    .option(
      t("Cast a spell (1200 Mana, Requires /element/ Lv11)"),
      {
        transform: (state) => {
          state = grantResource(Resource.Mana, -1200)(state);
          return state;
        },
        isEnabled: (state) => {
          return (
            getResourceAmount(state, Resource.Mana) >= 1200 &&
            getElementLevel(state, elementToUse(state)) >= 11
          );
        },
      },
      "step1",
    )
    .option(t("Maybe later"))
    .end();

  studentContestEventBuilder
    .message(
      t(`You cast your spell! The crowd goes silent and glances at your direction. You appear to have gotten the attention of the young man at the stage.
  
He exclaims, loudly: "Quite impressive!" But then challenges you again: "However... can you do *even better* than that?"

It looks like you are going to have to work harder...`),
    )
    .tag("step1")
    .option(
      t("Cast a stronger spell (1500 Mana, Requires /element/ Lv14)"),
      {
        transform: (state) => {
          state = grantResource(Resource.Mana, -1500)(state);
          return state;
        },
        isEnabled: (state) => {
          return (
            getResourceAmount(state, Resource.Mana) >= 1500 &&
            getElementLevel(state, elementToUse(state)) >= 14
          );
        },
      },
      "step2",
    )
    .option(t("Maybe later"))
    .end();

  studentContestEventBuilder
    .message(
      t(`With more concentration than last time, you unleash a new spell. It is much stronger this time. You hear the students wowing at you, amazed at your spell. Then the young man speaks again:
  
"We have a *winner*! Please come forward to the stage for your prize," he shouts. It looks like that did the trick!

You climb the stage. The man pulls out a small gold medal and wraps it around your neck. "Congratulations! Thank you for showing everyone how to truly use /element/!"`),
    )
    .tag("step2")
    .option(
      "Continue",
      {
        transform: (state) => studentContestStoryline.complete(state),
      },
      "final",
    )
    .end();

  studentContestEventBuilder
    .message(
      t(`Back at home, you show the medal to your hired wizard. She squeals and appears very excited. "That is *amazing*! You are so talented. I feel so honored to be working for you! And I'm sure others will too."
  
You put the gold medal in a case and decide to display it. You are a little proud. You don't think it is really going to help you in the short term. But reputation builds up over time - you believe this achievement will benefit whoever might be next to take over this house.

**You have completed the "Student Contest" Storyline! In future runs, you will get an extra 5 Wizards available - without the need to build Wizard Chambers for them - as soon as you meet your first Wizard!**`),
    )
    .tag("final");

  const studentContestEvent = studentContestEventBuilder.build();

  registerTransformation(
    [[TransformationTags.WizardAmount]],
    "studentContest",
    studentContestStoryline.getName(),
    TransformationType.Addition,
    (state) => (studentContestStoryline.isBonusActive(state) ? 5 : 0),
  );

  registerRandomEventTrigger(
    studentContestEvent,
    65,
    (state) =>
      !studentContestStoryline.isCompleted(state) &&
      !studentContestEvent.hasTriggered(state) &&
      getExplorationStatus(state) != ExplorationStatus.None &&
      getCurrentDungeonFloor(state) == FLOOR_FOR_ELEMENT[elementToUse(state)],
    (state) => ({ element: translateElement(elementToUse(state)) }),
  );
}

export { studentContestStoryline };
