import { formatNumber } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { ActionEffect } from "../../../action/Action";
import { AttackTarget } from "../../../exploration/AttackTarget";
import { calculatePlayerMaxHP } from "../../../exploration/CombatStats";
import {
  getCurrentPlayerHP,
  modifyPlayerCurrentHP,
} from "../../../exploration/Exploration";
import { pushCombatActionResult } from "../../../exploration/ExplorationActionResult";
import { GameState } from "../../../GameState";
import { isHealingPossible } from "../../../temporaryeffects/CommonTemporaryEffects";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { ItemParams } from "../../Item";
import { PouchItem } from "../PouchItem";

class Cherry extends PouchItem {
  getId(): string {
    return "cherry";
  }

  getPicture() {
    return require("./cherry.png");
  }

  getBaseName(params: ItemParams): string {
    return t("Cherry");
  }

  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "They say a cherry a day keeps the doctor away... wait, something is off. Restores a little :hp:.",
    );
  }

  hpHealTags = [];
  protected getBaseExtraItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    return {
      hpHeal: {
        value: 10,
        tags: this.hpHealTags,
        unit: ":hp:",
      },
    };
  }

  getHPHealAmount(state: GameState, params: ItemParams): number {
    return this.getItemEffects(state, params, "hpHeal").hpHeal;
  }

  getEffect(state: GameState, params: ItemParams): string {
    return t("^+{{amount}}^<{{explanation}}>:hp:", {
      amount: formatNumber(this.getHPHealAmount(state, params)),
      explanation: this.getItemEffectExplanations(state, params, "hpHeal")
        .hpHeal,
    });
  }

  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 30;
  }

  doPouchItemEffect(state: GameState, params: ItemParams): GameState {
    const healAmt = this.getHPHealAmount(state, params);
    state = modifyPlayerCurrentHP(state, healAmt, this.getBaseName(params));
    const actionResult = {
      id: Math.random().toString(),
      damage: -healAmt,
      isCrit: false,
      isHit: true,
      time: getSecondsPlayed(state),
      source: AttackTarget.Player,
      target: AttackTarget.Player,
      tags: this.getTags(),
    };
    state = pushCombatActionResult(state, actionResult);
    return state;
  }

  shouldAutoUse(state: GameState, params: ItemParams): boolean {
    const maxHP = calculatePlayerMaxHP(state);
    const currentHP = getCurrentPlayerHP(state);
    return (
      (maxHP - currentHP > this.getHPHealAmount(state, params) ||
        currentHP / maxHP < 0.3) &&
      isHealingPossible(state, AttackTarget.Player)
    );
  }

  getUsesPerCombatBase(params: ItemParams): number {
    return 3;
  }
}

const cherry = new Cherry();

export { cherry as Cherry };
