import { t } from "../../utils/I18n";
import { buildEvent } from "../events/EventMessageBuilder";
import { triggerEvent } from "../events/Events";
import { EventTag } from "../events/GameEvent";
import { Flag } from "../Flag";
import { SetFlagListeners } from "../Flags";
import { Storylines } from "../storylines/Storylines";
import { CombatClassesStoryline } from "./CombatClassesStoryline";

export function loadCombatClassesUnlocker() {
  Storylines.register(CombatClassesStoryline);
  const combatClassesUnlockEventBuilder = buildEvent(
    "typesOfWizards",
    t("(Storyline) Types of Wizards"),
    [EventTag.STORYLINE],
  ).setStorylineIds(CombatClassesStoryline.getId());

  combatClassesUnlockEventBuilder
    .message(
      t(`You exit the Tower of Training, eager to get back home to apply what you have just learned, but you are stopped in your tracks as soon as you step out of the door. "I'm sorry - there is one last thing." The familiar voice of the Head Instructor Giselle comes from behind. In her hand is a book. The title: *"Classes"*
    
"This was always meant to be the reward for finishing the training. It is a book on various wizard specializations. It almost slipped my mind, because I got carried away with the explanation about Enhancements."`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = CombatClassesStoryline.complete(state);
          state = CombatClassesStoryline.activateBonus(state);
          return state;
        },
      },
      "end",
    );
  combatClassesUnlockEventBuilder
    .message(
      t(`You accept the book, and you quickly skim through it. There are a few pictures: a Wizard wielding a large sword and a shield, another shooting a Fireball, yet another one building home furniture. And big, long pages of explanation for each of them, it appears.
      
You are not sure if you will have time to actually look at this or learn much out of it. But you think of keeping the book in your library anyhow...

**You have completed the "Types of Wizards" Storyline and permanently unlocked the ability to select a Class after retiring! Your choice of Class at the beginning of the game gives you a powerful bonus for the duration of that retirement; you can choose a new Class by retiring again.**`),
    )
    .tag("end");

  const combatClassesUnlockEvent = combatClassesUnlockEventBuilder.build();

  SetFlagListeners.register((state, flag) => {
    if (
      flag == Flag.EnhancementsUnlocked &&
      !combatClassesUnlockEvent.hasTriggered(state) &&
      !CombatClassesStoryline.isCompleted(state)
    ) {
      state = triggerEvent(combatClassesUnlockEvent)(state);
    }
    return state;
  });
}
