export const INITIAL_STATE: GameState = {
  global: GLOBAL_INITIAL_STATE,
  run: RUN_INITIAL_STATE,
  world: WORLD_INITIAL_STATE,
  options: OPTIONS_INITIAL_STATE,
  temp: createTempState(),
  isFake: false,
};

export function createTempState(): GameTempState {
  return {
    cache: new Map(),
    flagCache: new Map(),
    worldFlagCache: new Map(),
    locationEncyclopediaCache: undefined,
    isInAutocast: false,
    isInPermacast: false,
    isInPouch: false,
    elementLevelCache: {
      Fire: undefined,
      Earth: undefined,
      Water: undefined,
      Air: undefined,
      Poison: undefined,
      Mind: undefined,
      Life: undefined,
      Electric: undefined,
      Death: undefined,
      Holy: undefined,
      Time: undefined,
      Space: undefined,
    },
    lastAutoPurchaseRun: 0,
    lastAutoPurchaseEnhancementRun: 0,
    familiarLevelCache: new Map(),
    combatStatCache: {
      Player: {},
      Familiar: {},
      Enemy: {},
    },
    resourceCapCache: {},
    unlockedElementsCache: undefined,
    incomeOverTimeCache: {},
    autocastPointsRequirementCache: {},
    spellManaCostCache: {},
    researchPerSecCache: undefined,
    spellRequirementsCache: {},
    spellExpCache: {},
    lastProcessedAutocastSpellId: undefined,
  };
}

export function createHypotheticalState(state: GameState): GameState {
  const clonedState = clone(state);
  clonedState.temp = createTempState();
  clonedState.isFake = true;
  return clonedState;
}

export type StateContextValue = GameState & {
  apply: (transform: GameStateTransform) => void;
  overwrite: (transform: GameStateTransform) => void;
};

export const StateContext = React.createContext({
  ...clone(INITIAL_STATE),
  apply: (transform: GameStateTransform) => {},
  overwrite: (transform: GameStateTransform) => {},
});
