import { t } from "../../../../utils/I18n";
import {
  getWorldAccumulatorVariable,
  incrementAccumulatorVariable,
} from "../../../AccumulatorVariables";
import { DungeonFloors } from "../../../exploration/dungeons/Dungeons";
import {
  addEnemyDeathListener,
  getCurrentDungeonFloor,
  getSuccessfulExplorationsForFloor,
} from "../../../exploration/Exploration";
import { hasFlag, setFlag } from "../../../Flags";
import { GameState } from "../../../GameState";
import { addGameStateListener } from "../../../GameStateListeners";
import {
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../../../items/Inventory";
import { Items } from "../../../items/Items";
import { paper } from "../../../items/transmute/materials/Paper";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { LocationEncyclopedia } from "./LocationEncyclopediaStoryline";

export function loadLocationEncyclopedia() {
  Storylines.register(LocationEncyclopedia);
  const locationEncyclopediaEventBuilder = buildEvent(
    "locationEncyclopedia",
    t("(Storyline) Location Encyclopedia"),
    [EventTag.STORYLINE],
  ).setStorylineIds(LocationEncyclopedia.getId());

  locationEncyclopediaEventBuilder.message(
    t(`Exploring the Sewers is proving to be quite annoying. Having to deal with countless enemies and extreme disappointment every time you reach a dead end is... not good.
    
You wish you had a map - so you'd be able to take the correct path, without so much wasted time.`),
  );
  locationEncyclopediaEventBuilder
    .message(
      t(`Except, you don't know where you could get one.
      
Instead, you think about building a map yourself. It shouldn't save you effort, but maybe you could sell it to others. Or, even better - maybe someone else might find a better use for it?

You think about what you would need to make such a map - or rather, an *encyclopedia* of those places. You'll certainly need Paper, and some Shards for gluing. But you know how to make that by now...`),
    )
    .option(
      t("Craft a map (20 Paper, 10 Poison Shards)"),
      {
        transform: (state) => {
          state = removeFromInventoryWithAnyParams(paper, 20, state);
          state = removeFromInventoryWithAnyParams(
            Items.getById("elementalShardPoison"),
            10,
            state,
          );
          state = LocationEncyclopedia.complete(state);
          state = tallyAll(state);
          return state;
        },
        isEnabled: (state) =>
          getTotalAmountOfItem(paper, state) >= 20 &&
          getTotalAmountOfItem(Items.getById("elementalShardPoison"), state) >=
            10,
      },
      "end",
    )
    .option(t("Maybe later"))
    .end();
  locationEncyclopediaEventBuilder
    .message(
      t(`You put together all the pages and proceed to carefully glue them. You wrap it with a makeshift book cover, and write a grand name: "Location Encyclopedia". You chuckle - it is just a blank book!
    
But it won't be blank for long. You start scribbling information that you still remember about the areas you have already visited. You are very hopeful this will be useful in the future!

**You have completed the "Location Encyclopedia" Storyline! Unlocking the boss of a location will reduce the amount of required Exploration on that area by 35% after retiring. This will stack every retirement you clear the location, to a minimum of 1 exploration required!**`),
    )
    .tag("end")
    .end();

  const locationEncyclopediaEvent = locationEncyclopediaEventBuilder.build();

  addGameStateListener("locationEncyclopediaListener", (state) => {
    if (
      !locationEncyclopediaEvent.hasTriggered(state) &&
      !LocationEncyclopedia.isCompleted(state) &&
      getSuccessfulExplorationsForFloor(state, "sewerHazardousSector") >= 5
    ) {
      return triggerEvent(locationEncyclopediaEvent);
    }
  });

  function tallyAll(state: GameState): GameState {
    if (LocationEncyclopedia.isCompleted(state)) {
      if (state.temp.locationEncyclopediaCache == undefined) {
        state.temp.locationEncyclopediaCache = [].concat(
          DungeonFloors.getAll(),
        );
      }
      const remainingFloors = state.temp.locationEncyclopediaCache;

      let floorsToTally = [];
      let talliedFloors = [];

      for (let floorIdx = 0; floorIdx < remainingFloors.length; floorIdx++) {
        const floor = remainingFloors[floorIdx];
        const isTallied = hasFlag(
          state,
          "locationEncyclopediaTally-" + floor.getIdForTallyingClears(),
        );
        if (isTallied) {
          talliedFloors.push(floor);
          continue;
        }
        if (
          !isTallied &&
          floor.isUnlocked(state) &&
          floor.canFightBoss(state)
        ) {
          floorsToTally.push(floor);
        }
      }

      if (talliedFloors.length > 0) {
        state.temp.locationEncyclopediaCache = remainingFloors.filter(
          (floor) => !talliedFloors.includes(floor),
        );
      }

      if (floorsToTally.length > 0) {
        for (let floor of floorsToTally) {
          state = incrementAccumulatorVariable(
            state,
            "clearedTally-" + floor.getIdForTallyingClears(),
            1,
          );
          state = setFlag(
            "locationEncyclopediaTally-" + floor.getIdForTallyingClears(),
          )(state);
        }
      }
    }
    return state;
  }

  addEnemyDeathListener(locationEncyclopediaEvent.getId(), (state, _enemy) => {
    if (LocationEncyclopedia.isCompleted(state)) {
      const floor = getCurrentDungeonFloor(state);

      const isTallied = hasFlag(
        state,
        "locationEncyclopediaTally-" + floor.getIdForTallyingClears(),
      );
      if (!isTallied && floor.isUnlocked(state) && floor.canFightBoss(state)) {
        state = incrementAccumulatorVariable(
          state,
          "clearedTally-" + floor.getIdForTallyingClears(),
          1,
        );
        state = setFlag(
          "locationEncyclopediaTally-" + floor.getIdForTallyingClears(),
        )(state);
      }
    }
    return state;
  });

  registerTransformation(
    [[TransformationTags.ExplorationBossRequirement]],
    "locationEncyclopedia",
    LocationEncyclopedia.getName(),
    TransformationType.Multiplier,
    (state, params) => {
      if (LocationEncyclopedia.isBonusActive(state)) {
        const tally = getWorldAccumulatorVariable(
          state,
          "clearedTally-" + params.floor.getIdForTallyingClears(),
        );
        return Math.pow(0.65, tally);
      }
      return 1.0;
    },
  );
}
