import { t } from "../../../../utils/I18n";
import { getAccumulatorVariable } from "../../../AccumulatorVariables";
import { AttackTarget } from "../../../exploration/AttackTarget";
import {
  clearCombatStatCache,
  CombatStat,
} from "../../../exploration/CombatStats";
import KnightInShiningArmor from "../../../exploration/enemies/training/KnightInShiningArmor";
import {
  modifyPlayerCurrentHP,
  startCombat,
} from "../../../exploration/Exploration";
import { hasFlag, setFlag } from "../../../Flags";
import { addGameStateListener } from "../../../GameStateListeners";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { addUICommand } from "../../../UICommands";
import { buildEvent } from "../../EventMessageBuilder";
import { getGameEventById, triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

const howToFightHPStoryline = new Storyline(
  "howToFightHP",
  t("How to Fight (HP)"),
  140,
  10,
  (state) =>
    getGameEventById("howToFight").hasTriggered(state)
      ? t(
          "Lose in battle at least 20 times; however, you cannot trigger it again this run",
        )
      : t("Lose in battle at least 20 times"),
  t("+30 Max:hp:"),
  (_state) => true,
);

const howToFightAttackStoryline = new Storyline(
  "howToFightAttack",
  t("How to Fight (Attack)"),
  141,
  10,
  (state) =>
    getGameEventById("howToFight").hasTriggered(state)
      ? t(
          "Lose in battle at least 20 times; however, you cannot trigger it again this run",
        )
      : t("Lose in battle at least 20 times"),
  t("+10:attack:"),
  (_state) => true,
);

const howToFightDefenseStoryline = new Storyline(
  "howToFightDefense",
  t("How to Fight (Defense)"),
  142,
  10,
  (state) =>
    getGameEventById("howToFight").hasTriggered(state)
      ? t(
          "Lose in battle at least 20 times; however, you cannot trigger it again this run",
        )
      : t("Lose in battle at least 20 times"),
  t("+10:defense:"),
  (_state) => true,
);

const howToFightAttackDelayStoryline = new Storyline(
  "howToFightAttackDelay",
  t("How to Fight (Speed)"),
  143,
  10,
  (state) =>
    getGameEventById("howToFight").hasTriggered(state)
      ? t(
          "Lose in battle at least 20 times; however, you cannot trigger it again this run",
        )
      : t("Lose in battle at least 20 times"),
  t("-5%:attackDelay:"),
  (_state) => true,
);

export function loadHowToFight() {
  Storylines.register(howToFightHPStoryline);
  Storylines.register(howToFightAttackStoryline);
  Storylines.register(howToFightDefenseStoryline);
  Storylines.register(howToFightAttackDelayStoryline);

  const howToFightEventBuilder = buildEvent(
    "howToFight",
    t("(Storyline) How to Fight"),
    [EventTag.STORYLINE],
  ).setStorylineIds([
    howToFightHPStoryline.getId(),
    howToFightAttackStoryline.getId(),
    howToFightDefenseStoryline.getId(),
    howToFightAttackDelayStoryline.getId(),
  ]);

  howToFightEventBuilder.message(
    t(`*Ouch*. You have had to run away from combat so many times. As you nurse your wounds yet again at home, you hear a knock on the door. Your hired wizard checks it out, but she exclaims in a dreamy voice: "It is a *knight in shining armor*!"
    
You feel she is probably just pulling your leg, so you decide to head up to the door and open it. But she was not. She was speaking the truth. You come face to face with what you can just describe as... a knight in shining armor.`),
  );
  howToFightEventBuilder.message(
    t(
      `"Salutations," he says, removing his helmet. "I am a combat trainer, and I have observed your recent battle." He continues: "I know some more advanced ways to fight, which can definitely help level the playfield. I can teach some of them to you, if you'd like."
      
You agree that would be a good idea. You ask him about prices, but he shakes his head: "I don't take pleasure in benefiting from those in need." You don't really understand this person. But their armor and movements appear very skilled, so you're inclined to believe him.

Your hired wizard watches you, from a distance, but she seems more enthralled with the knight than anything else.`,
    ),
  );
  howToFightEventBuilder
    .message(
      t(`"However", he says, "you will first need to show me that you can at least handle some basic combat. Let's have a short duel - *don't worry, I will not use my full strength* - and if you are able to win, I'll teach you some advanced combat techniques."
    
You are now wary of him. He looks very strong. Is this really going to be possible?

"I will heal you before we begin, but otherwise, make sure you are ready for combat before we duel", he finishes saying, before putting his helmet back on. You heed his words, and ready up for combat.`),
    )
    .option(t("Fight the Knight in Shining Armor"), {
      transform: (state) => {
        state = addUICommand(state, "goToExploration");
        state = addUICommand(state, "closeAllMenus");
        state = addUICommand(state, "closeEventOverlay");
        state = modifyPlayerCurrentHP(
          state,
          Infinity,
          t("Healing from the Knight in Shining Armor"),
        );
        state = startCombat(KnightInShiningArmor)(state);
        return state;
      },
    })
    .option(t("Maybe later"))
    .end();

  howToFightEventBuilder
    .message(
      t(`The fighting feels intense. You hear your hired wizard rooting for the knight, but you are too focused on the fight to argue with her.
    
You wipe your sweat from your face and ready to strike again at the knight, but he tells you to stop. "That's enough," he shouts. You back off, and take a deep breath, putting down your weapon just as he does.`),
    )
    .tag("afterFight");

  howToFightEventBuilder
    .message(
      t(`The knight removes his helmet once again, and tells you: "That was a pretty good job. I'd be happy to teach you now." You feel like you need to continue releasing the tension from the fight. You are not sure how he can be so unfazed.
    
He continues: "Please tell me what kind of combat techniques you would like to learn."`),
    )
    .option(
      t("More vitality (Max HP)"),
      {
        transform: (state) => {
          state = howToFightHPStoryline.complete(state);
          state = howToFightHPStoryline.activateBonus(state);
          state = clearCombatStatCache(state, AttackTarget.Player);
          return state;
        },
        isVisible: (state) => !howToFightHPStoryline.isCompleted(state),
      },
      "maxHP",
    )
    .option(
      t("More power (Attack)"),
      {
        transform: (state) => {
          state = howToFightAttackStoryline.complete(state);
          state = howToFightAttackStoryline.activateBonus(state);
          state = clearCombatStatCache(state, AttackTarget.Player);
          return state;
        },
        isVisible: (state) => !howToFightAttackStoryline.isCompleted(state),
      },
      "attack",
    )
    .option(
      t("More resilience (Defense)"),
      {
        transform: (state) => {
          state = howToFightDefenseStoryline.complete(state);
          state = howToFightDefenseStoryline.activateBonus(state);
          state = clearCombatStatCache(state, AttackTarget.Player);
          return state;
        },
        isVisible: (state) => !howToFightDefenseStoryline.isCompleted(state),
      },
      "defense",
    )
    .option(
      t("More speed (Attack Delay)"),
      {
        transform: (state) => {
          state = howToFightAttackDelayStoryline.complete(state);
          state = howToFightAttackDelayStoryline.activateBonus(state);
          state = clearCombatStatCache(state, AttackTarget.Player);
          return state;
        },
        isVisible: (state) =>
          !howToFightAttackDelayStoryline.isCompleted(state),
      },
      "attackDelay",
    )
    .end();

  howToFightEventBuilder
    .message(
      t(`You tell the trainer you would like to be more healthy throughout and outside of battle. With a nod, he proceeds to tell you how to better take care of your body to endure more wounds and be able to heal quicker. You listen intently and take notes.
    
After the exchange, you thank him for his services. With those tips, you are sure you will be much more healthy in the future.

**You have completed the "How to Fight (HP)" Storyline! You will permanently gain +30 Max:hp:. This will persist even after retirement!**`),
    )
    .tag("maxHP")
    .end();
  howToFightEventBuilder
    .message(
      t(`You tell the trainer you would like to be more powerful and able to deal more damage to enemies with your attacks. With a nod, he proceeds to tell you how to better swing your weapon and position yourself in order to apply more power to your movement. You listen intently and take notes.
    
After the exchange, you thank him for his services. With those tips, you are sure you will be able to gain an upper hand over enemies in the future.

**You have completed the "How to Fight (Attack)" Storyline! You will permanently gain +10:attack:. This will persist even after retirement!**`),
    )
    .tag("attack")
    .end();
  howToFightEventBuilder
    .message(
      t(`You tell the trainer you would like to be more resilient against enemy attacks. With a nod, he proceeds to tell you how to strengthen your muscles and harden your body to become more impervious to damage. You listen intently and take notes.
    
After the exchange, you thank him for his services. With those tips, you are sure you will be able to endure onslaughts much more effectively.

**You have completed the "How to Fight (Defense)" Storyline! You will permanently gain +10:defense:. This will persist even after retirement!**`),
    )
    .tag("defense")
    .end();
  howToFightEventBuilder
    .message(
      t(`You tell the trainer you would like to be speedier in your movements in combat. With a nod, he proceeds to tell you how to gracefully switch positions from one to the next and how to make your attacks flow into the next one effortlessly. You listen intently and take notes.
    
After the exchange, you thank him for his services. With those tips, you are sure you will be able to approach combat with increased agility.

**You have completed the "How to Fight (Speed)" Storyline! You will permanently gain -5%:attackDelay:. This will persist even after retirement!**`),
    )
    .tag("attackDelay")
    .end();

  const howToFightEvent = howToFightEventBuilder.build();

  addGameStateListener("howToFightEventTriggerer", (state) => {
    // For perf
    if (hasFlag(state, "howToFightIsDone")) {
      return;
    }
    if (
      [
        howToFightHPStoryline,
        howToFightAttackStoryline,
        howToFightDefenseStoryline,
        howToFightAttackDelayStoryline,
      ].every((storyline) => storyline.isCompleted(state))
    ) {
      return setFlag("howToFightIsDone");
    }

    if (
      !howToFightEvent.hasTriggered(state) &&
      getAccumulatorVariable(state, "deathCount") >= 20 &&
      [
        howToFightHPStoryline,
        howToFightAttackStoryline,
        howToFightDefenseStoryline,
        howToFightAttackDelayStoryline,
      ].some((storyline) => !storyline.isCompleted(state))
    ) {
      state = setFlag("howToFightIsDone")(state);
      return triggerEvent(howToFightEvent);
    }
  });

  registerTransformation(
    [[AttackTarget.Player, CombatStat.MaxHP]],
    "howToFightHP",
    howToFightHPStoryline.getName(),
    TransformationType.Addition,
    (state) => (howToFightHPStoryline.isBonusActive(state) ? 30 : 0),
  );
  registerTransformation(
    [[AttackTarget.Player, CombatStat.Attack]],
    "howToFightAttack",
    howToFightAttackStoryline.getName(),
    TransformationType.Addition,
    (state) => (howToFightAttackStoryline.isBonusActive(state) ? 10 : 0),
  );
  registerTransformation(
    [[AttackTarget.Player, CombatStat.Defense]],
    "howToFightDefense",
    howToFightDefenseStoryline.getName(),
    TransformationType.Addition,
    (state) => (howToFightDefenseStoryline.isBonusActive(state) ? 10 : 0),
  );
  registerTransformation(
    [[AttackTarget.Player, CombatStat.AttackDelay]],
    "howToFightAttackDelay",
    howToFightAttackDelayStoryline.getName(),
    TransformationType.Multiplier,
    (state) =>
      howToFightAttackDelayStoryline.isBonusActive(state) ? 0.95 : 1.0,
  );
}

export {
  howToFightAttackDelayStoryline,
  howToFightAttackStoryline,
  howToFightDefenseStoryline,
  howToFightHPStoryline,
};
