import React, { useEffect, useState } from "react";
import { StyleSheet, View, useWindowDimensions } from "react-native";
import { Card, RadioButton } from "react-native-paper";
import { clearAllCaches } from "../backend/Caching";
import { isDemo } from "../backend/Demo";
import {
  Challenges,
  getVisibleChallenges,
} from "../backend/challenges/Challenges";
import {
  CombatClasses,
  getSelectableCombatClasses,
} from "../backend/classes/CombatClasses";
import { DualFlexibilityStoryline } from "../backend/events/storyline/cavern/DualFlexibilityStoryline";
import {
  SpellElement,
  getMaxPrimaryElementLevel,
  getUnlockedElements,
  grantStartingExp,
  selectPrimaryElement,
  selectSecondaryElement,
  translateElement,
} from "../backend/spells/Elements";
import { runGameStartupListeners } from "../backend/spells/GameStartup";
import { LOAD_FROM_FILE_SUPPORTED } from "../utils/FilesystemSave";
import { t } from "../utils/I18n";
import { Button } from "./utility/Button";
import { Card as UtilityCard } from "./utility/Card";
import LoadGameContainer from "./utility/LoadGameContainer";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

export default function Intro() {
  const state = useGameState();
  const [selectedElement, setSelectedElement] = useState(null as string | null);
  const elements = getUnlockedElements(state);

  const selectedMPL =
    selectedElement != null
      ? getMaxPrimaryElementLevel(state, selectedElement as SpellElement)
      : 0;
  const isFirstTime =
    (state.world.totalTimesResetted ?? 0) === 0 &&
    (state.global.newGamePlusCount ?? 0) === 0;
  const showLoadGameContainer = isFirstTime && !isDemo();

  const loadText = LOAD_FROM_FILE_SUPPORTED
    ? t(
        "If you have save data from the demo version of the game, you may load it by opening the save file, or pasting it here.",
      )
    : t(
        "If you have save data from the demo version of the game, you may load it by pasting it here.",
      );
  const primarySchoolText = t(`Please choose a primary element.`);

  const elementDescriptionMap: Record<SpellElement, string> = {
    Fire: t(
      "You have selected **Fire**! Fire specializes in high temperatures and raw power, as well as Catalysts, which increase the efficiency with which you can generate resources.",
    ),
    Earth: t(
      "You have selected **Earth**! Earth is defensive and strongly protective. It specializes in Storages, which allow you to hold more resources at a time.",
    ),
    Water: t(
      "You have selected **Water**! Water has a focus on health and basic Mana generation, the source of all Magic. Allows for more frequent spellcasting.",
    ),
    Air: t(
      "You have selected **Air**! Air is speedy, but also plentiful. It is great for making large amounts of Coins, efficiently and quickly, as well as moving fast.",
    ),
    Poison: t(
      "You have selected **Poison**! Poison can deal damage over time regardless of defense (and is sometimes even unavoidable), and is a key ingredient in many alchemical potions, Monstium gathering, and more.",
    ),
    Mind: t(
      "You have selected **Mind**! Mind is a mysterious element with multiple applications, affecting various things about the game. It is also good for guaranteed damage that doesn't miss.",
    ),
    Life: t(
      "You have selected **Life**! Apparently there is some of this element in each living being, from the tiniest plant to the largest animal. Key for healing and staying healthy.",
    ),
    Electric: t(
      "You have selected **Electric**! Energy in one of its purest forms. Powerful, fast attacks, with a high critical hit chance that can pierce defenses.",
    ),
    Death: t(
      "You have selected **Death**! One of the strongest elements, it is intensely powerful, but those who delve in it too much are said to become corrupt... It allows game-changing Rituals.",
    ),
    Holy: t(
      "You have selected **Holy**! Power granted directly from a Divine, it can purify corruption.",
    ),
    Time: t(
      "You have selected **Time**! A secret element, very few know about it.",
    ),
    Space: t(
      "You have selected **Space**! A secret element, very few know about it.",
    ),
  };

  const [selectedClassId, setSelectedClassId] = useState(
    undefined as string | undefined,
  );
  const classes = getSelectableCombatClasses(state);
  const showClassSelection = classes.length > 0;
  const selectedClass =
    selectedClassId != null ? CombatClasses.getById(selectedClassId) : null;

  const [selectedSecondaryElement, setSelectedSecondaryElement] = useState(
    null as string | null,
  );
  const selectedSecondaryMPL =
    selectedSecondaryElement != null
      ? getMaxPrimaryElementLevel(
          state,
          selectedSecondaryElement as SpellElement,
        )
      : 0;
  const secondarySchoolText = t(
    "Please choose a second primary element. It will gain identical bonuses to the first.",
  );
  const showSecondaryElementSelection =
    DualFlexibilityStoryline.isBonusActive(state);

  useEffect(() => setSelectedSecondaryElement(null), [selectedElement]);

  const [selectedChallengeId, setSelectedChallengeId] = useState(
    undefined as string | undefined,
  );
  const challenges = getVisibleChallenges(state);
  const showChallenges = challenges.length > 0;
  const selectedChallenge =
    selectedChallengeId != null
      ? Challenges.getById(selectedChallengeId)
      : null;

  const allowMovingForward =
    selectedElement != null &&
    (!showClassSelection || selectedClassId != null) &&
    (!showSecondaryElementSelection || selectedSecondaryElement != null);

  const { height, width } = useWindowDimensions();

  return (
    <View style={[styles.container, width < 600 && { width }]}>
      <View>
        <View>
          <Text h1>{t("Welcome to Magic Research 2!")}</Text>
          <Text style={Styles.mt8}>
            {t(
              "In Magic Research 2, you are a rookie wizard with a single ambition: to find or create the Philosopher's Stone, a legendary magical item that is said to be able to cure any illness. As you do so, you will learn more than you ever imagined about Magic and about the world around you. What kinds of adventures await you?",
            )}
          </Text>
          <Text style={Styles.mt8}>
            {t(
              "Before starting your story, you will need to choose a primary element. While you will need to utilize every element at your disposal to succeed, choosing an element as a primary will make it much easier to become proficient on that element, and might allow you to access some specific content related to that element.",
            )}
          </Text>
        </View>
        <UtilityCard style={Styles.mt16}>
          <Card.Title title={t("Elements")} accessibilityRole="header" />
          <Card.Content>
            <Markdown>{primarySchoolText}</Markdown>
            <View style={Styles.mt8}>
              <RadioButton.Group
                onValueChange={setSelectedElement}
                value={selectedElement ?? ""}
              >
                {elements.map((element, idx) => (
                  <RadioButton.Item
                    value={element}
                    key={element}
                    label={translateElement(element)}
                  />
                ))}
              </RadioButton.Group>
            </View>
            <View style={Styles.mt8}>
              {selectedElement != undefined && (
                <View>
                  <Markdown>{elementDescriptionMap[selectedElement]}</Markdown>
                  {selectedMPL > 0 ? (
                    <Text style={Styles.mt8}>
                      {t(
                        "Your Maximum Primary Level (MPL) for this element is {{mpl}}.",
                        { mpl: selectedMPL },
                      )}
                    </Text>
                  ) : (
                    false
                  )}
                </View>
              )}
            </View>
            {showSecondaryElementSelection && selectedElement != undefined && (
              <View style={Styles.mt16}>
                <Markdown>{secondarySchoolText}</Markdown>
                <View style={Styles.mt8}>
                  <RadioButton.Group
                    onValueChange={setSelectedSecondaryElement}
                    value={selectedSecondaryElement ?? ""}
                  >
                    {elements
                      .filter((element) => element != selectedElement)
                      .map((element, idx) => (
                        <RadioButton.Item
                          value={element}
                          key={element}
                          label={translateElement(element)}
                        />
                      ))}
                  </RadioButton.Group>
                </View>
                <View style={Styles.mt8}>
                  {selectedSecondaryElement != undefined && (
                    <View>
                      <Markdown>
                        {elementDescriptionMap[selectedSecondaryElement]}
                      </Markdown>
                      {selectedSecondaryMPL > 0 ? (
                        <Text style={Styles.mt8}>
                          {t(
                            "Your Maximum Primary Level (MPL) for this element is {{mpl}}.",
                            { mpl: selectedSecondaryMPL },
                          )}
                        </Text>
                      ) : (
                        false
                      )}
                    </View>
                  )}
                </View>
              </View>
            )}
          </Card.Content>
        </UtilityCard>
        {showClassSelection && (
          <UtilityCard style={Styles.mt16}>
            <Card.Title title={t("Classes")} accessibilityRole="header" />
            <Card.Content>
              <Markdown>{t("Choose a class.")}</Markdown>
              <View style={Styles.mt8}>
                <RadioButton.Group
                  onValueChange={setSelectedClassId}
                  value={selectedClassId ?? ""}
                >
                  {classes.map((combatClass, idx) => (
                    <RadioButton.Item
                      value={combatClass.getId()}
                      key={combatClass.getId()}
                      label={combatClass.getDisplayName()}
                    />
                  ))}
                </RadioButton.Group>
              </View>
              <View style={Styles.mt8}>
                {selectedClass != null && (
                  <View>
                    <View>
                      <Markdown>
                        {selectedClass.getDisplayDescription(state)}
                      </Markdown>
                    </View>
                    <View>
                      <Markdown>
                        {t("Effect: **{{combatClassEffect}}**", {
                          combatClassEffect:
                            selectedClass.getDisplayEffect(state),
                        })}
                      </Markdown>
                    </View>
                  </View>
                )}
              </View>
            </Card.Content>
          </UtilityCard>
        )}
        {showChallenges && (
          <UtilityCard style={Styles.mt16}>
            <Card.Title title={t("Challenges")} accessibilityRole="header" />
            <Card.Content>
              <Markdown>
                {t(
                  "You may optionally select a challenge for the current retirement. Challenges strongly increase the difficulty of your journey; however, if you are able to complete them, you will gain permanent bonuses!",
                )}
              </Markdown>
              <View style={Styles.mt8}>
                <RadioButton.Group
                  onValueChange={setSelectedChallengeId}
                  value={selectedChallengeId ?? ""}
                >
                  <RadioButton.Item
                    value={""}
                    key={"none"}
                    label={t("No Challenge")}
                  />
                  {challenges.map((challenge, idx) => (
                    <RadioButton.Item
                      value={challenge.getId()}
                      key={challenge.getId()}
                      label={t("{{challenge}}{{completed}}", {
                        challenge: challenge.getName(),
                        completed: challenge.isCompleted(state)
                          ? t(" (Complete!)")
                          : "",
                      })}
                    />
                  ))}
                </RadioButton.Group>
              </View>
              <View style={Styles.mt8}>
                {selectedChallenge != null && (
                  <View>
                    <View>
                      <Markdown>
                        {t("{{description}} **{{effect}}**", {
                          description: selectedChallenge.getDescription(state),
                          effect: selectedChallenge.getEffect(state),
                        })}
                      </Markdown>
                    </View>
                    <View style={Styles.mt4}>
                      <Markdown>
                        {t("To win: **{{objective}}**", {
                          objective: selectedChallenge.getObjective(state),
                        })}
                      </Markdown>
                    </View>
                    {selectedChallenge.isCompleted(state) && (
                      <View style={Styles.mt4}>
                        <Markdown>
                          {t(
                            "*You have already completed this Challenge!* It is giving you the following permanent bonus: **{{reward}}**",
                            {
                              reward: selectedChallenge.getReward(state),
                            },
                          )}
                        </Markdown>
                      </View>
                    )}
                  </View>
                )}
              </View>
            </Card.Content>
          </UtilityCard>
        )}
        <Button
          style={styles.startButton}
          disabled={!allowMovingForward}
          onPress={() => {
            state.apply((state) => {
              state.run.combatClassId = selectedClassId;
              state.run.currentChallengeId = selectedChallengeId;
              state.run.lastProcessedTime = Date.now();
              state = selectPrimaryElement(selectedElement as SpellElement)(
                state,
              );
              if (selectedSecondaryElement != null) {
                state = selectSecondaryElement(
                  selectedSecondaryElement as SpellElement,
                )(state);
              }
              // Grant the starting exp. Must be done after selecting primary elements
              // due to Primary challenge.
              state = grantStartingExp(state);
              state = runGameStartupListeners(state);
              // Clear all caches just in case
              state = clearAllCaches(state);
              return state;
            });
          }}
        >
          {t("Start the Game!")}
        </Button>
        {showLoadGameContainer && (
          <View>
            <Markdown>{loadText}</Markdown>
            <LoadGameContainer style={Styles.mt8} />
          </View>
        )}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",

    maxWidth: 600,
    paddingHorizontal: 16,
    paddingTop: 8,
  },
  buttonGroupText: {
    fontSize: 14,
  },
  startButton: {
    marginVertical: 24,
  },
  mplText: {
    marginTop: 8,
  },
});
