import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import {
  LayoutChangeEvent,
  ScrollView,
  StyleSheet,
  View,
  useWindowDimensions,
} from "react-native";
import { TouchableWithoutFeedback } from "react-native-gesture-handler";
import { ActivityIndicator, useTheme } from "react-native-paper";
import { clearFlag, hasFlag } from "../backend/Flags";
import { AttackTarget } from "../backend/exploration/AttackTarget";
import { calculatePlayerMaxHP } from "../backend/exploration/CombatStats";
import {
  endExploration,
  getCurrentDungeonFloor,
  getCurrentEnemyHP,
  getCurrentFamiliarHP,
  getCurrentPlayerHP,
  getSuccessfulExplorationsForFloor,
  pauseExploration,
  resumeExploration,
  startBossExploration,
  startExplorationOnFloor,
} from "../backend/exploration/Exploration";
import { ExplorationStatus } from "../backend/exploration/ExplorationStatus";
import { getExplorationStatus } from "../backend/exploration/ExplorationUtils";
import {
  hasCurrentActiveCompanion,
  hasCurrentCompanion,
} from "../backend/exploration/Summoning";
import { Dungeon } from "../backend/exploration/dungeons/Dungeon";
import { DungeonFloor } from "../backend/exploration/dungeons/DungeonFloor";
import {
  getUnlockedDungeons,
  isDungeonFloorCompletedThisRun,
} from "../backend/exploration/dungeons/Dungeons";
import { getCurrentEnemy } from "../backend/exploration/enemies/Enemies";
import {
  FamiliarCompanionStatusType,
  getCurrentCompanionClass,
  getCurrentCompanionData,
} from "../backend/familiars/Familiar";
import { shouldShowGrayscaleFamiliar } from "../backend/rituals/NecromancyRitual";
import { formatNumber } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import { EnemyActionDisplay } from "./exploration/EnemyActionDisplay";
import { EnemyHPBar } from "./exploration/EnemyHPBar";
import { EnemySpriteTile } from "./exploration/EnemySpriteTile";
import { ExplorationBottomComponent } from "./exploration/ExplorationBottomComponent";
import { ExplorationProgressDisplay } from "./exploration/ExplorationProgressDisplay";
import { FamiliarActionDisplay } from "./exploration/FamiliarActionDisplay";
import { FamiliarHPBar } from "./exploration/FamiliarHPBar";
import { FamiliarSpriteTile } from "./exploration/FamiliarSpriteTile";
import { PlayerActionDisplay } from "./exploration/PlayerActionDisplay";
import { PlayerHPBar } from "./exploration/PlayerHPBar";
import { PlayerMessageLog } from "./exploration/PlayerMessageLog";
import { AccessibilityContext } from "./utility/AccessibilityContext";
import { BottomResourceViewer } from "./utility/BottomResourceViewer";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import Divider from "./utility/Divider";
import { GameStateFreezer } from "./utility/GameStateFreezer";
import { KeyboardListener } from "./utility/KeyboardListener";
import Styles from "./utility/Styles";
import { TemporaryEffectsViewer } from "./utility/TemporaryEffectsViewer";
import { Text } from "./utility/Text";
import { UIContext } from "./utility/UIContext";
import { VisibilityContext } from "./utility/VisibilityContext";
import { GameIcon } from "./utility/icons/GameIcon";
import { useGameState } from "./utility/useGameState";

function ExplorationRow(props: { floor: DungeonFloor }) {
  const floor = props.floor;
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const isBossUnlocked =
    floor.canFightBoss(state) && floor.getBoss(state) != null;
  const isFloorCompleted = isDungeonFloorCompletedThisRun(state, floor);
  const theme = useTheme();
  return useMemo(
    () => (
      <View>
        <View style={styles.noneRow}>
          <View>
            <Text style={styles.noneFloorText}>{floor.getFloorName()}</Text>
            <View style={styles.noneStatusRow}>
              <Text>{t("Status: ")}</Text>
              {isBossUnlocked ? (
                <MaterialIcons
                  name="check"
                  size={24}
                  style={styles.noneIcon}
                  color={
                    isFloorCompleted
                      ? theme.dark
                        ? theme.colors.primaryContainer
                        : theme.colors.primary
                      : theme.colors.onBackground
                  }
                />
              ) : (
                <MaterialIcons
                  name="lock-open"
                  size={24}
                  style={styles.noneIcon}
                  color={theme.colors.onBackground}
                />
              )}
              <Text>
                {isFloorCompleted
                  ? t("Completed")
                  : isBossUnlocked
                  ? t("Boss Unlocked")
                  : t("Open for Exploration")}
              </Text>
            </View>
          </View>
          <Button
            onPress={() =>
              stateRef.current.apply((state) => {
                state = startExplorationOnFloor(state, floor);
                if (floor.getNextFloor(state)?.isUnlocked?.(state)) {
                  state.run.autoAdvanceToNextFloor = false;
                }
                return state;
              })
            }
          >
            {t("Explore")}
          </Button>
        </View>
      </View>
    ),
    [isFloorCompleted, isBossUnlocked, theme, floor.getFloorName()],
  );
}

function ExplorationDungeon(props: { dungeon: Dungeon }) {
  const state = useGameState();
  const unlockedFloors = props.dungeon
    .getDungeonFloors()
    .filter((floor) => floor.isUnlocked(state));
  return useMemo(
    () => (
      <CategoryContainer title={props.dungeon.getName()}>
        <View style={Styles.mt8}>
          {/* {props.dungeon.hasTriggeredEvent(state) && (
          <View style={Styles.mb8}>
            <Text style={styles.noMoreStorylines}>
              {t(
                "No more Storylines can be found by exploring these areas this run!",
              )}
            </Text>
          </View>
        )} */}
          {unlockedFloors.map((floor) => {
            return <ExplorationRow key={floor.getId()} floor={floor} />;
          })}
        </View>
      </CategoryContainer>
    ),
    [JSON.stringify(unlockedFloors.map((floor) => floor.getId()))],
  );
}

function ExplorationNone() {
  const state = useGameState();
  const allDungeons = getUnlockedDungeons(state);
  return useMemo(
    () => (
      <ScrollView
        style={styles.container}
        contentContainerStyle={styles.noneContent}
      >
        <View style={styles.topRow}>
          <Text h2>{t("Exploration")}</Text>
        </View>
        <Text>{t("Select a destination.")}</Text>
        {allDungeons.map((dungeon) => {
          return <ExplorationDungeon key={dungeon.getId()} dungeon={dungeon} />;
        })}
      </ScrollView>
    ),
    [JSON.stringify(allDungeons.map((dungeon) => dungeon.getId()))],
  );
}

function ExplorationMain() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const explorationStatus = getExplorationStatus(state);

  const dungeonFloor = getCurrentDungeonFloor(state);
  const dungeonTitle = dungeonFloor.getFloorName();
  const currentEnemySprite =
    explorationStatus == ExplorationStatus.Combat &&
    getCurrentEnemy(state).getPicture(state);
  const toUnlockBoss =
    dungeonFloor.getExplorationSuccessesForBoss(state) -
    getSuccessfulExplorationsForFloor(state, dungeonFloor.getId());
  const currentEnemyName =
    explorationStatus == ExplorationStatus.Combat &&
    getCurrentEnemy(state).getName();

  const checkmark =
    getSuccessfulExplorationsForFloor(state, dungeonFloor.getId()) >=
      dungeonFloor.getExplorationSuccessesForBoss(state) ||
    isDungeonFloorCompletedThisRun(state, dungeonFloor);
  const hasBoss = dungeonFloor.getBoss(state) != null;
  const canFightBoss = dungeonFloor.canFightBoss(state);
  const canMoveToNextFloor = dungeonFloor
    .getNextFloor(state)
    ?.isUnlocked(state);

  const fightingBoss =
    explorationStatus == ExplorationStatus.Combat &&
    getCurrentEnemy(state).isBoss();

  const isExplorationPaused = hasFlag(state, "exploration_paused");
  const togglePauseExploration = useCallback(() => {
    stateRef.current.apply((state) =>
      hasFlag(state, "exploration_paused")
        ? resumeExploration(state)
        : pauseExploration(state),
    );
  }, []);
  const pauseExplorationButton = useMemo(
    () => (
      <Button
        style={styles.pauseExplorationButton}
        compact={true}
        onPress={togglePauseExploration}
        disableDisabledRenderingOptimization={true}
      >
        {isExplorationPaused ? t("Resume Exploration") : t("Pause Exploration")}
      </Button>
    ),
    [isExplorationPaused, togglePauseExploration],
  );

  const explorationStatusContainer = useMemo(() => {
    let explorationStatusContainer = null;
    if (explorationStatus == ExplorationStatus.Combat) {
      explorationStatusContainer = (
        <ScrollView
          centerContent={false}
          style={styles.explorationStatusContainer}
          persistentScrollbar={true}
        >
          <Text h3>{currentEnemyName}</Text>
          <View style={Styles.mt4}>
            <EnemyHPBar />
          </View>
          <View style={Styles.mt4}>
            <EnemyActionDisplay />
          </View>
          <View style={Styles.mt4}>
            <TemporaryEffectsViewer
              target={AttackTarget.Enemy}
              skipPermacast={true}
              accessibilityHeader={t("Enemy Buffs")}
            />
          </View>
        </ScrollView>
      );
    } else if (explorationStatus == ExplorationStatus.Exploring) {
      explorationStatusContainer = (
        <View style={styles.explorationStatusContainer}>
          <View style={Styles.mb8}>
            <Text h3>{t("Exploring...")}</Text>
          </View>
          <ExplorationProgressDisplay />
          {pauseExplorationButton}
        </View>
      );
    } else if (explorationStatus == ExplorationStatus.ExploringBoss) {
      explorationStatusContainer = (
        <View style={styles.explorationStatusContainer}>
          <Text h3>{t("Preparing for Boss...")}</Text>
          <ExplorationProgressDisplay />
          {pauseExplorationButton}
        </View>
      );
    } else if (explorationStatus == ExplorationStatus.Reviving) {
      explorationStatusContainer = (
        <View style={styles.explorationStatusContainer}>
          <Text h3>{t("Recovering...")}</Text>
          <ActivityIndicator />
        </View>
      );
    }
    return explorationStatusContainer;
  }, [explorationStatus, currentEnemyName, pauseExplorationButton]);

  const hasCompanion = hasCurrentCompanion(state);
  const currentCompanionSprite =
    hasCompanion && getCurrentCompanionClass(state).getPicture(state);
  const isCompanionReady =
    hasCompanion &&
    [
      FamiliarCompanionStatusType.Combat,
      FamiliarCompanionStatusType.Exploring,
    ].includes(getCurrentCompanionData(state)?.status?.companionStatus);
  const flipCompanionRow = useCallback(() => {
    stateRef.current.apply((state) => {
      state.run.exploration.familiarIsInBackRow =
        !state.run.exploration.familiarIsInBackRow;
      return state;
    });
  }, []);

  const isFloorCompleted = isDungeonFloorCompletedThisRun(state, dungeonFloor);
  const theme = useTheme();

  const backdropColor =
    explorationStatus == ExplorationStatus.Combat &&
    getCurrentEnemy(state).getBackdropColor(state);

  const accessibilityContext = useContext(AccessibilityContext);

  const visible = useContext(VisibilityContext);
  useEffect(() => {
    stateRef.current.apply(clearFlag("combat_paused_magic_tap"));
    return () => stateRef.current.apply(clearFlag("combat_paused_magic_tap"));
  }, [visible]);

  const uiContext = useContext(UIContext);
  const currentEventOccurrenceId = uiContext.getUIState(
    "current_event_occurrence_id",
  );
  const currentExplorationBottomTab =
    uiContext.getUIState("exploration_bottom_component_tab") ?? "";

  useEffect(() => {
    if (visible && currentEventOccurrenceId == null) {
      if (explorationStatus === ExplorationStatus.Exploring) {
        accessibilityContext.announceForAccessibility(t("Exploring"));
      }

      if (explorationStatus === ExplorationStatus.ExploringBoss) {
        accessibilityContext.announceForAccessibility(t("Preparing for boss"));
      }

      if (explorationStatus === ExplorationStatus.Combat) {
        accessibilityContext.announceForAccessibility(
          t("Fighting {{enemy}}", { enemy: currentEnemyName }),
        );
      }

      if (explorationStatus === ExplorationStatus.Reviving) {
        accessibilityContext.announceForAccessibility(
          t("You were defeated. Recovering from injuries..."),
        );
      }
    }
  }, [
    explorationStatus,
    accessibilityContext,
    visible,
    currentEventOccurrenceId,
  ]);

  const [lastAnnounced, setLastAnnounced] = useState("");

  const onKeyboard = useCallback(
    (ev: KeyboardEvent) => {
      if (!visible) {
        return;
      }

      const state = stateRef.current;

      if (ev.shiftKey && ev.ctrlKey && ev.key == "Z") {
        const inCombat =
          getExplorationStatus(state) == ExplorationStatus.Combat;
        const hasFamiliar = hasCurrentActiveCompanion(state);
        if (lastAnnounced === "player" && hasFamiliar) {
          const currentHP = getCurrentFamiliarHP(state);
          const maxHP =
            getCurrentCompanionClass(state)?.getStatsForCompanion(state)
              .maxHP ?? 0;
          accessibilityContext.announceForAccessibility(
            t("Familiar HP: {{current}}/{{max}}", {
              current: formatNumber(Math.ceil(currentHP), {
                showDecimals: false,
              }),
              max: formatNumber(Math.ceil(maxHP), {
                showDecimals: false,
              }),
            }),
          );
          setLastAnnounced("familiar");
        } else if (
          (lastAnnounced === "player" ||
            (hasFamiliar && lastAnnounced === "familiar")) &&
          inCombat
        ) {
          const currentHP = getCurrentEnemyHP(state);
          const maxHP = getCurrentEnemy(state).getMaxHP(state);
          accessibilityContext.announceForAccessibility(
            t("Enemy HP: {{current}}/{{max}}", {
              current: formatNumber(Math.ceil(currentHP), {
                showDecimals: false,
              }),
              max: formatNumber(Math.ceil(maxHP), {
                showDecimals: false,
              }),
            }),
          );
          setLastAnnounced("enemy");
        } else {
          accessibilityContext.announceForAccessibility(
            t("Player HP: {{current}}/{{max}}", {
              current: formatNumber(Math.ceil(getCurrentPlayerHP(state)), {
                showDecimals: false,
              }),
              max: formatNumber(Math.ceil(calculatePlayerMaxHP(state)), {
                showDecimals: false,
              }),
            }),
          );
          setLastAnnounced("player");
        }
      }
    },
    [visible, lastAnnounced],
  );

  const isCompanionUndead = shouldShowGrayscaleFamiliar(state);

  const buttonRow = useMemo(
    () => (
      <View style={styles.row}>
        <Button
          style={styles.exitButtonContainer}
          compact={true}
          onPress={() => stateRef.current.apply(endExploration)}
        >
          {t("Exit")}
        </Button>
        <Button
          style={styles.exitButtonContainer}
          compact={true}
          onPress={() =>
            stateRef.current.apply((state) =>
              startExplorationOnFloor(
                state,
                getCurrentDungeonFloor(state).getNextFloor(
                  state,
                ) as DungeonFloor,
              ),
            )
          }
          disabled={!canMoveToNextFloor}
        >
          {t("Next Floor")}
        </Button>
        <Button
          compact={true}
          onPress={() => stateRef.current.apply(startBossExploration)}
          disabled={!canFightBoss || fightingBoss}
        >
          {t("Fight Boss")}
        </Button>
      </View>
    ),
    [canFightBoss, fightingBoss, canMoveToNextFloor],
  );

  const topContainer = useMemo(
    () => (
      <View>
        <View style={[styles.row, styles.enemyContainer]}>
          {explorationStatusContainer}
          <View style={Styles.ml8}>
            <EnemySpriteTile
              sprite={currentEnemySprite}
              backdropColor={backdropColor}
            />
          </View>
        </View>
        <Divider />
        {hasCompanion && !state.run.exploration.familiarIsInBackRow && (
          <View
            key="companion"
            style={[styles.row, styles.summonContainer, { marginTop: 8 }]}
          >
            <FamiliarSpriteTile
              sprite={currentCompanionSprite}
              showDead={!isCompanionReady}
              showUndead={isCompanionUndead}
            />
            <View style={{ flex: 1, marginLeft: 4, marginRight: 4 }}>
              <FamiliarHPBar />
            </View>
            <FamiliarActionDisplay />
          </View>
        )}
        <View
          style={[
            styles.row,
            styles.playerContainer,
            (!hasCompanion || state.run.exploration.familiarIsInBackRow) && {
              marginTop: 8,
            },
          ]}
        >
          <View style={{ flex: 1, marginRight: 4 }}>
            <PlayerHPBar />
          </View>
          <PlayerActionDisplay />
        </View>
        {hasCompanion && state.run.exploration.familiarIsInBackRow && (
          <View key="companion" style={[styles.row, styles.summonContainer]}>
            <FamiliarSpriteTile
              sprite={currentCompanionSprite}
              showDead={!isCompanionReady}
              showUndead={isCompanionUndead}
            />
            <View style={{ flex: 1, marginLeft: 4, marginRight: 4 }}>
              <FamiliarHPBar />
            </View>
            <FamiliarActionDisplay />
          </View>
        )}
        {hasCompanion && (
          <View style={styles.switchFamiliarRowContainer}>
            <View style={styles.switchFamiliarRow}>
              <TouchableWithoutFeedback
                onPress={flipCompanionRow}
                accessibilityLabel={
                  state.run.exploration.familiarIsInBackRow
                    ? t("Send Familiar to Front Row")
                    : t("Send Familiar to Back Row")
                }
                hitSlop={{ top: 8, bottom: 8, left: 8, right: 8 }}
              >
                <GameIcon icon="exchange" small />
              </TouchableWithoutFeedback>
            </View>
          </View>
        )}
        <View style={[styles.row, styles.runAway]}>
          <View style={[styles.row, styles.unlockBossContainer]}>
            {checkmark && (
              <MaterialIcons
                name="check"
                size={24}
                style={styles.checkmark}
                color={
                  isFloorCompleted
                    ? theme.dark
                      ? theme.colors.primaryContainer
                      : theme.colors.primary
                    : theme.colors.onBackground
                }
              />
            )}
            <View style={styles.unlockBossTextContainer}>
              <Text doNotScaleFonts={true} numberOfLines={2}>
                {isFloorCompleted
                  ? t("Floor complete!")
                  : canFightBoss
                  ? t("Boss unlocked")
                  : hasBoss
                  ? t("To unlock boss: {{amount}}", {
                      amount: formatNumber(toUnlockBoss),
                    })
                  : ""}
              </Text>
            </View>
          </View>
          {buttonRow}
        </View>
      </View>
    ),
    [
      currentEnemySprite,
      explorationStatusContainer,
      toUnlockBoss,
      checkmark,
      canFightBoss,
      hasBoss,
      stateRef,
      startBossExploration,
      endExploration,
      hasCompanion,
      currentCompanionSprite,
      isFloorCompleted,
      backdropColor,
      isCompanionReady,
      isCompanionUndead,
      state.run.exploration.familiarIsInBackRow,
      flipCompanionRow,
      buttonRow,
      theme,
    ],
  );

  const { height, width } = useWindowDimensions();

  const [containerHeight, setContainerHeight] = useState(null);

  const onLayout = useCallback((event: LayoutChangeEvent) => {
    setContainerHeight(event.nativeEvent.layout.height);
  }, []);

  return useMemo(
    () => (
      <View style={styles.wholeContainer} onLayout={onLayout}>
        <View style={styles.container}>
          <View style={[styles.topRow]}>
            <Text h2>
              {t("Exploration - {{dungeonTitle}}", { dungeonTitle })}
            </Text>
          </View>
          <View style={styles.restOfScreen}>
            {topContainer}
            <View style={styles.playerMessageLogContainer}>
              {currentExplorationBottomTab == "" && (
                <View
                  style={styles.accessibilityHidden}
                  accessible={true}
                  accessibilityLabel={t("Exploration Log Tab")}
                  accessibilityRole="header"
                  accessibilityElementsHidden={false}
                />
              )}
              <PlayerMessageLog />
            </View>
            {width < 640 &&
              height > 640 &&
              !state.options.hideBottomResourceViewer && (
                <GameStateFreezer isVisible={currentExplorationBottomTab == ""}>
                  <BottomResourceViewer />
                </GameStateFreezer>
              )}
          </View>
        </View>
        <View style={styles.bottomComponentContainer}>
          <ExplorationBottomComponent containerHeight={containerHeight} />
        </View>
        <KeyboardListener callback={onKeyboard} />
      </View>
    ),
    [
      stateRef,
      currentExplorationBottomTab,
      endExploration,
      dungeonTitle,
      topContainer,
      theme,
      onKeyboard,
      width,
      height,
      state.options.hideBottomResourceViewer,
      onLayout,
      containerHeight,
    ],
  );
}

export default function Exploration() {
  const state = useGameState();

  const explorationStatus = getExplorationStatus(state);

  if (explorationStatus == ExplorationStatus.None) {
    return <ExplorationNone />;
  }

  // We are exploring, reviving, or in combat
  return <ExplorationMain />;
}

const styles = StyleSheet.create({
  wholeContainer: { flex: 1 },
  container: {
    flex: 1,
    padding: 8,
  },
  topRow: {
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "center",
    marginBottom: 8,
    height: 32,
  },
  arrowBack: {
    marginRight: 8,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  checkmark: {
    marginRight: 4,
  },
  enemyContainer: {
    marginBottom: 8,
  },
  playerContainer: {
    marginTop: 16,
  },
  summonContainer: {
    marginTop: 16,
  },
  playerMessageLogContainer: {
    flex: 1,
    padding: 8,
    borderColor: "#444",
    borderWidth: StyleSheet.hairlineWidth,
    marginTop: 8,
  },
  moveRow: {
    marginTop: 8,
  },
  exitButtonContainer: { marginRight: 4 },
  explorationStatusContainer: {
    flex: 1,
    maxHeight: 144,
  },
  restOfScreen: {
    flex: 1,
    justifyContent: "space-between",
    marginBottom: 48,
  },
  bottomComponentContainer: {
    position: "absolute",
    left: 0,
    bottom: 0,
    width: "100%",
  },
  dungeonButton: {
    marginTop: 8,
  },
  gradient: {
    left: 0,
    top: 0,
    width: "100%",
    height: "60%",
    position: "absolute",
    opacity: 0.3,
  },
  noneContent: {
    paddingBottom: 32,
  },
  noneDungeonContentsContainer: {
    marginTop: 8,
  },
  noneRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    paddingVertical: 4,
  },
  noneStatusRow: {
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "center",
  },
  noneFloorText: {
    fontWeight: "bold",
  },
  noneIcon: {
    marginHorizontal: 4,
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  runAway: {
    marginTop: 8,
  },
  unlockBossContainer: { flex: 1, justifyContent: "flex-start" },
  unlockBossTextContainer: { flex: 1 },
  accessibilityHidden: {
    width: 1,
    height: 1,
    position: "absolute",
    left: 0,
    top: 0,
  },
  pauseExplorationButton: {
    marginTop: 16,
    alignSelf: "flex-start",
  },
  noMoreStorylines: {
    fontStyle: "italic",
  },
  switchFamiliarRow: {
    position: "absolute",
    top: -40,
    cursor: "pointer",
  },
  switchFamiliarRowContainer: {
    alignSelf: "center",
  },
});
