import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import {
  assignSpellcraftToVat,
  canApplySpellcraftToSpell,
} from "../../backend/spellcraft/SpellcraftUtils";
import { Spellcrafts } from "../../backend/spellcraft/Spellcrafts";
import { t } from "../../utils/I18n";
import { ItemTile } from "../inventory/ItemTile";
import { Button } from "../utility/Button";
import { CostViewer } from "../utility/CostViewer";
import { EffectViewer } from "../utility/Miscellaneous";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";

export function SpellcraftInfo(props: {
  spellcraftId: string;
  showTile: boolean;
}) {
  const state = useGameState();

  const spellcraft = Spellcrafts.getById(props.spellcraftId);
  const item = spellcraft.getBaseItem();
  const cost = {
    resources: {},
    items: {},
  };
  cost.items[item.getId()] = 1000;

  const theme = useTheme();

  const effect = spellcraft.getEffect(state);

  return useMemo(
    () => (
      <View>
        <View style={styles.itemDetailsContainer}>
          <View style={styles.main}>
            <Text h3>{item.getDefaultName()}</Text>
            {effect && <EffectViewer>{effect}</EffectViewer>}
          </View>
          {props.showTile && (
            <ItemTile
              title={spellcraft.getName()}
              icon={item.getId()}
              tooltipIdSuffix="spellcraftInfo"
              borderColor={theme.colors.outline}
            />
          )}
        </View>
        <View style={styles.costContainer}>
          <View style={Styles.mr8}>
            <Text>{t("Cost:")}</Text>
          </View>
          <CostViewer resources={cost.resources} items={cost.items} />
        </View>
      </View>
    ),
    [item.getId(), effect, JSON.stringify(cost), theme],
  );
}

export function SpellcraftDetails(props: {
  vatId: string;
  spellcraftId: string;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const vatId = props.vatId;
  const spellcraft = Spellcrafts.getById(props.spellcraftId);
  const item = spellcraft.getBaseItem();

  const cost = {
    resources: {},
    items: {},
  };
  cost.items[item.getId()] = 1000;

  const canAfford = spellcraft.canAfford(state);

  const assignCallback = useCallback(() => {
    stateRef.current.apply((state) =>
      assignSpellcraftToVat(state, vatId, props.spellcraftId),
    );
  }, [item]);

  const vat = state.run.spellcrafts[vatId];

  const isAlreadyAssigned = vat.spellcraftId == props.spellcraftId;
  const hasError = !canApplySpellcraftToSpell(state, vat.spellId, spellcraft);

  const theme = useTheme();

  return useMemo(
    () => (
      <View style={styles.craftingDetailsContainer}>
        <SpellcraftInfo spellcraftId={props.spellcraftId} showTile={true} />
        <View style={styles.actionRow}>
          <Button
            key="assignButton"
            disabled={!canAfford || isAlreadyAssigned || hasError}
            onPress={assignCallback}
          >
            {t("Spellcraft")}
          </Button>
        </View>
        {!isAlreadyAssigned && hasError && (
          <View style={Styles.mt8}>
            <Text style={{ color: theme.colors.error, fontStyle: "italic" }}>
              {t(
                "You cannot assign this Spellcraft to the current spell! You may have only up to 2 separate Spellcrafts on a single spell. They must both be different, and unless the Spellcraft used is from items that you can Transmute using your Primary Element, the Spellcraft can only be used once across all spells!",
              )}
            </Text>
          </View>
        )}
      </View>
    ),
    [
      props.spellcraftId,
      canAfford,
      assignCallback,
      isAlreadyAssigned,
      hasError,
      theme,
    ],
  );
}

const styles = StyleSheet.create({
  craftingDetailsContainer: {
    marginBottom: 16,
  },
  actionRow: {
    flexDirection: "row",
    flexWrap: "wrap",
    marginTop: 8,
    gap: 8,
  },
  actionButtonContainer: {
    marginRight: 8,
  },
  costContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start",
    marginTop: 4,
  },
  costTextContainer: {
    marginRight: 8,
  },
  inventoryFullContainer: {
    flex: 1,
    marginHorizontal: 8,
  },
  cannotCraftText: {
    fontStyle: "italic",
  },
  itemDetailsContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start",
  },
  main: {
    flex: 1,
  },
});
