import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { ActionEffect } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { AttackTarget } from "../../exploration/AttackTarget";
import { GameState } from "../../GameState";
import { stunned, stunProtection } from "../../temporaryeffects/Stunning";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../temporaryeffects/TemporaryEffects";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";

class Knockout extends CombatSpellBase {
  getId(): string {
    return "knockout";
  }

  getSpellName(): string {
    return t("Knockout");
  }

  getElement(): SpellElement {
    return SpellElement.Earth;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.COMBAT_BUFF;
  }

  getBaseManaCost(state: GameState): number {
    return 2500;
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Apply a single, solid impact to the enemy. As they say, if everything else fails...",
    );
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      duration: {
        value: 1,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    return t(
      'Inflicts "Stunned!" for ^{{duration}}^<{{durationExplanation}}> sec',
      {
        duration: formatNumber(effects.duration, { showDecimals: true }),
        durationExplanation: explanations.duration,
      },
    );
  }

  doSpellAction(state: GameState): GameState {
    const effects = this.getActionEffects(state, "duration");
    state = grantTemporaryEffect(stunned.getId(), effects.duration, {
      target: AttackTarget.Enemy,
    })(state);
    return state;
  }

  getBaseCooldown(): number {
    return 120;
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Earth: 28,
    };
  }

  isCastingRecommended(state: GameState): boolean {
    return (
      super.isCastingRecommended(state) &&
      !hasTemporaryEffect(state, stunProtection.getId(), AttackTarget.Enemy)
    );
  }

  allowRegisteringTemporaryEffectsAsPermacast(): boolean {
    return false;
  }
}

export default new Knockout();
