import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { CombatActionResultListeners } from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import { SpellElement } from "../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  clearTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Wet extends TemporaryEffect {
  getId(): string {
    return "wet";
  }
  getDisplayName(): string {
    return t("Wet");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "+10%:attackDelay:; next Fire attack received is halved in damage",
    );
  }
}

const wet = new Wet();

export { wet as Wet };

export function loadWet() {
  TemporaryEffects.register(wet);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "wet",
    wet.getDisplayName(),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(state, wet.getId(), params.target) &&
        (params.tags ?? []).includes(SpellElement.Fire)
        ? 0.5
        : 1.0;
    },
  );

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.AttackDelay, target]],
      "wet" + target,
      wet.getDisplayName(),
      TransformationType.Multiplier,
      (state, params) => {
        return hasTemporaryEffect(state, wet.getId(), target) ? 1.1 : 1.0;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    wet.getId(),
    CombatStat.AttackDelay,
  );

  CombatActionResultListeners.register((state, result) => {
    if (
      result.tags.includes(SpellElement.Fire) &&
      result.isHit &&
      hasTemporaryEffect(state, wet.getId(), result.target)
    ) {
      state = clearTemporaryEffect(wet.getId(), result.target)(state);
    }
    return state;
  });
}
