import { keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../action/ActionAreas";
import { areFamiliarsUnlocked } from "../familiars/Familiar";
import { slimeFamiliar } from "../familiars/SlimeFamiliar";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import { SpellElement } from "../spells/Elements";
import { Building, BuildingSpell } from "./Building";
import { BuildingAmountListeners, getBuildingAmount } from "./Buildings";

class FamiliarCrate extends Building {
  getId(): string {
    return "familiarCrate";
  }

  getName(): string {
    return t("Familiar Crate");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Dedicate a parcel of land to a Familiar - a forever friend and companion to a skilled Wizard.",
    );
  }

  getDisplayEffect(state: GameState): string {
    return t(
      "+1 Familiars. Note: Cannot be demolished - release the Familiar instead from the Familiars screen",
    );
  }

  getBaseLandRequired(): number {
    return 25;
  }

  canTurnOff(): boolean {
    return false;
  }
}

export const familiarCrate = new FamiliarCrate();

class BuildFamiliarCrate extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.OTHER] };
  }

  getBuilding(): Building {
    return familiarCrate;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Coins: 10000,
      LifeEssence: 10000,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      Coins: 10.0,
      LifeEssence: 10.0,
    };
  }

  isVisible(state: GameState): boolean {
    return areFamiliarsUnlocked(state) && super.isVisible(state);
  }

  shouldHideDemolish(): boolean {
    return true;
  }

  canDemolish(state: GameState, amount: number): boolean {
    return false;
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Life;
  }
}

export const buildFamiliarCrate = new BuildFamiliarCrate();

function familiarEqualizer(state: GameState) {
  const buildingAmt = getBuildingAmount(state, familiarCrate);
  const familiarAmt = keysOf(state.run.familiars).length;
  if (familiarAmt < buildingAmt) {
    const buildingAmt = getBuildingAmount(state, familiarCrate);
    let familiarAmt = keysOf(state.run.familiars).length;
    while (familiarAmt < buildingAmt) {
      state = slimeFamiliar.create(state);
      familiarAmt++;
    }
  }
  return state;
}

BuildingAmountListeners.register(familiarEqualizer, "familiarCrates");
