import { valuesOf } from "../../utils/CoreUtils";
import { GameState } from "../GameState";
import { FamiliarData, Familiars, FamiliarStatusType } from "./Familiar";
import { FamiliarClass } from "./FamiliarClass";

export function getCurrentCompanionId(state: GameState): string {
  return getCurrentCompanionData(state)?.id;
}

export function getCurrentCompanionData(
  state: GameState,
): FamiliarData | undefined {
  return valuesOf(state.run.familiars).find(
    (familiarData) =>
      familiarData.status.status === FamiliarStatusType.Companion,
  );
}

export function getCurrentCompanionClass(
  state: GameState,
): FamiliarClass | undefined {
  const companionData = getCurrentCompanionData(state);
  if (companionData == null) {
    return undefined;
  }

  return Familiars.getById(companionData.familiarClassId);
}
