import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { poison } from "./Poison";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class PoisonImmunity extends TemporaryEffect {
  getId(): string {
    return "poisonImmunity";
  }
  getDisplayName(): string {
    return t("Poison Immunity");
  }
  getIcon() {
    return require("./poisonImmunity.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t('Immune to "Poison" debuff');
  }
}

export const poisonImmunity = new PoisonImmunity();

export function loadPoisonImmunity() {
  TemporaryEffects.register(poisonImmunity);

  registerTemporaryEffectListener(
    "poisonImmunityPoisonPrevention",
    (state, temporaryEffect) => {
      if (temporaryEffect.id != poison.getId()) {
        return temporaryEffect;
      }
      if (
        !hasTemporaryEffect(
          state,
          poisonImmunity.getId(),
          temporaryEffect.target,
        )
      ) {
        return temporaryEffect;
      }
      // Prevent!
      temporaryEffect.endTime = getSecondsPlayed(state) - 1.0;
      return temporaryEffect;
    },
  );
}
