import { formatTime } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { getResourceAmount, grantResource, Resource } from "../../Resources";
import { recordTimePieceConsumption } from "../../timetick/TimePieces";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Action, ActionEffect, DoActionArgs } from "../Action";
import { ActionArea, ActionSubcategories } from "../ActionAreas";

class UseTimePiecesWarp extends Action {
  getId(): string {
    return "useTimePiecesWarp";
  }

  isVisible(state: GameState): boolean {
    return getResourceAmount(state, Resource.TimePiece) > 0;
  }

  getDisplayName(): string {
    return t("Use Time Pieces (Warp)");
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategories[]>> {
    return { HOME: [ActionSubcategories.TIME_PIECES] };
  }

  getDisplayDescription(): string {
    return "Use your time pieces to warp time, affecting every aspect of the game.";
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      timePiecePerWarpedSec: {
        value: 5,
        tags: [TransformationTags.TimePiecePerWarpedSec],
      },
    };
  }

  getDisplayEffect(state: GameState): string {
    const cost = this.getCost(state).resources[Resource.TimePiece] ?? 1;
    const effect = this.getActionEffects(state);
    const totalSecs = cost / effect.timePiecePerWarpedSec;
    return t("Warp time forward by {{formattedTime}} (Real World)", {
      formattedTime: formatTime(totalSecs, {
        shortForm: false,
        showDecimals: false,
      }),
    });
  }

  canAfford(state: GameState): boolean {
    return (
      super.canAfford(state) &&
      getResourceAmount(state, Resource.TimePiece) >= 3000
    );
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        TimePiece: 3000,
      },
      items: {},
    };
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }
    const cost = this.getCost(state).resources.TimePiece ?? 1;
    const effect = this.getActionEffects(state);
    const totalSecs = cost / effect.timePiecePerWarpedSec;
    state = grantResource(Resource.TimePiece, -cost)(state);
    state = recordTimePieceConsumption(state, cost);
    state.run.warpTimeSecs = totalSecs;

    return state;
  }

  canSpam(): boolean {
    return false;
  }
}

export default new UseTimePiecesWarp();
