import { loadActionsCommonLogic, registerAction } from "./Actions";
import ExpandHome from "./actions/ExpandHome";
import GatherMana from "./actions/GatherMana";
import UseTimePiecesEvent from "./actions/UseTimePiecesEvent";
import UseTimePiecesStudy from "./actions/UseTimePiecesStudy";
import UseTimePiecesWarp from "./actions/UseTimePiecesWarp";

export function loadActions() {
  loadActionsCommonLogic();
  registerAction(GatherMana);
  registerAction(ExpandHome);
  registerAction(UseTimePiecesWarp);
  registerAction(UseTimePiecesStudy);
  registerAction(UseTimePiecesEvent);
}
