import { LinearGradient } from "expo-linear-gradient";
import React from "react";
import { Image, ImageSourcePropType, StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import { sanitizeImageSourceForElectron } from "../utility/icons/IconUtils";

export function EnemySpriteTile(props: {
  sprite?: ImageSourcePropType;
  backdropColor?: string | false;
}) {
  const LinearGradientComponent = LinearGradient;
  const theme = useTheme();
  let sprite = sanitizeImageSourceForElectron(props.sprite);
  return (
    <View style={[styles.tile, { borderColor: theme.colors.outlineVariant }]}>
      {props.backdropColor && (
        <LinearGradientComponent
          colors={[`${props.backdropColor}`, `${props.backdropColor}0`]}
          style={styles.gradient}
        />
      )}
      {sprite && (
        <Image resizeMode="contain" source={sprite} style={styles.image} />
      )}
    </View>
  );
}

const styles = StyleSheet.create({
  tile: {
    borderWidth: StyleSheet.hairlineWidth,
    height: 144,
    width: 144,
    justifyContent: "center",
    alignItems: "stretch",
  },
  image: {
    height: 142,
    width: 142,
  },
  gradient: {
    height: 142,
    width: 142,
    position: "absolute",
    left: 0,
    top: 0,
    opacity: 0.2,
  },
});
