import { openURL } from "expo-linking";
import { GameState } from "../backend/GameState";
import { loadBase64Data, serializeSaveData } from "../backend/SaveData";
import { isElectron } from "./ElectronUtils";
import { t } from "./I18n";

export type LoadResult =
  | {
      result: "success";
      data: GameState;
    }
  | {
      result: "cancel";
    }
  | {
      result: "error";
      error: string;
    };

export type TranslationsLoadResult =
  | {
      result: "success";
      data: Record<string, string>;
    }
  | {
      result: "cancel";
    }
  | {
      result: "error";
      error: string;
    };

export type SaveResult =
  | {
      result: "success";
    }
  | {
      result: "cancel";
    }
  | {
      result: "error";
      error: string;
    };

export const SAVE_TO_FILE_SUPPORTED = true;
export const LOAD_FROM_FILE_SUPPORTED = isElectron();

export async function loadSaveFromFile(): Promise<LoadResult> {
  if (!isElectron()) {
    return { result: "error", error: "Not implemented" };
  }

  try {
    // @ts-expect-error window.showOpenFilePicker() is available in Electron
    const [fileHandle] = await window.showOpenFilePicker();
    if (fileHandle != null) {
      const file = await fileHandle.getFile();
      const resultContents = await file.text();
      const unserialized = await loadBase64Data(resultContents);
      if (unserialized != null) {
        return { result: "success", data: unserialized };
      }
      return {
        result: "error",
        error: t("Failed decompressing and decoding the file"),
      };
    }
  } catch (e) {
    return {
      result: "error",
      error: t(
        "Found unexpected error reading from the filesystem or deserializing",
      ),
    };
  }
  return { result: "cancel" };
}

export async function loadTranslationsFromFile(): Promise<TranslationsLoadResult> {
  if (!isElectron()) {
    return { result: "error", error: "Not implemented" };
  }

  try {
    // @ts-expect-error window.showOpenFilePicker() is available in Electron
    const [fileHandle] = await window.showOpenFilePicker();
    if (fileHandle != null) {
      const file = await fileHandle.getFile();
      const resultContents = await file.text();
      try {
        const unserialized = JSON.parse(resultContents);
        if (unserialized != null) {
          return { result: "success", data: unserialized };
        }
        return {
          result: "error",
          error: t("Unknown error parsing the file"),
        };
      } catch (e) {
        return {
          result: "error",
          error: t("Error parsing the translation file"),
        };
      }
    }
  } catch (e) {
    return {
      result: "error",
      error: t("Found unexpected error reading from the filesystem"),
    };
  }
  return { result: "cancel" };
}

export async function saveToFile(state: GameState): Promise<SaveResult> {
  const serializedData = await serializeSaveData(state);
  await openURL("data:application/octet-stream;utf8," + serializedData);
  return { result: "success" };
}
