import { useEffect, useRef } from "react";
import memoize from "sonic-memoize";

export function clone<T>(obj: T): T {
  return JSON.parse(JSON.stringify(obj));
}

export async function sleep(ms: number) {
  return new Promise((resolve, _reject) => setTimeout(() => resolve(null), ms));
}

export function hashCode(str: string, seed: number = 1): number {
  let h1 = 0xdeadbeef ^ seed,
    h2 = 0x41c6ce57 ^ seed;
  for (let i = 0, ch; i < str.length; i++) {
    ch = str.charCodeAt(i);
    h1 = Math.imul(h1 ^ ch, 2654435761);
    h2 = Math.imul(h2 ^ ch, 1597334677);
  }

  h1 =
    Math.imul(h1 ^ (h1 >>> 16), 2246822507) ^
    Math.imul(h2 ^ (h2 >>> 13), 3266489909);
  h2 =
    Math.imul(h2 ^ (h2 >>> 16), 2246822507) ^
    Math.imul(h1 ^ (h1 >>> 13), 3266489909);

  return 4294967296 * (2097151 & h2) + (h1 >>> 0);
}

export function getRandomElementFromArray<T>(arr: T[]): T {
  const len = arr.length;
  const idx = Math.floor(Math.random() * len);
  return arr[idx];
}

export function compareVersions(
  v1: string,
  v2: string,
  maxDepth?: number,
): number {
  const v1Parts = v1.split(".");
  const v2Parts = v2.split(".");
  try {
    for (let i in v1Parts) {
      if (maxDepth != null && Number.parseInt(i) >= maxDepth) {
        break;
      }
      const v1Part = Number.parseInt(v1Parts?.[i] ?? "0");
      const v2Part = Number.parseInt(v2Parts?.[i] ?? "0");
      if (v1Part - v2Part != 0) {
        return v1Part - v2Part;
      }
    }
  } catch (e) {
    // Failed to compare versions. Pretend they are equal
    return 0;
  }
  return 0;
}

export function keysOf<T extends string | number | symbol, V>(
  obj: Partial<Record<T, V>> | Record<T, V>,
): T[] {
  return Object.keys(obj) as T[];
}

export const keysOfCached = memoize(keysOf);

export function valuesOf<T extends string | number | symbol, V>(
  obj: Partial<Record<T, V>> | Record<T, V>,
): V[] {
  return Object.values(obj) as V[];
}

export const valuesOfCached = memoize(valuesOf);

export function fromEntries<T extends string | number | symbol, V>(
  entries: [T, V][],
): Record<T, V> {
  return Object.fromEntries(entries) as Record<T, V>;
}

export function addRecords<T extends string | number | symbol>(
  records: Partial<Record<T, number>>[],
): Partial<Record<T, number>> {
  const result: Partial<Record<T, number>> = {};
  for (let record of records) {
    for (let key in record) {
      if (result[key] == null) {
        result[key] = 0;
      }
      result[key] = (record[key] ?? 0) + (result[key] ?? 0);
    }
  }
  return result;
}

export function nullthrows<T>(obj: T | null | undefined): T {
  if (obj == null) {
    throw new Error("Should be nonnull");
  }
  return obj;
}

export function useComponentDidUpdate(effect: () => void, dependencies) {
  const hasMounted = useRef(false);

  useEffect(() => {
    if (!hasMounted.current) {
      hasMounted.current = true;
      return;
    }
    effect();
  }, dependencies);
}

export function assertUnreachable(x: never): never {
  throw new Error("Didn't expect to get here");
}
