import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Petrified extends TemporaryEffect {
  getId(): string {
    return "petrified";
  }
  getDisplayName(): string {
    return t("Petrified");
  }
  isBeneficial(): boolean {
    return false;
  }
  getIcon() {
    return require("./petrified.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "All explicit attack damage received is reduced to 0; Attack gauge does not increase",
    );
  }
}

export const petrified = new Petrified();

export function loadPetrified() {
  TemporaryEffects.register(petrified);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "petrified",
    petrified.getDisplayName(),
    TransformationType.Override,
    (state, params) => {
      return hasTemporaryEffect(state, petrified.getId(), params.target)
        ? 0
        : undefined;
    },
  );

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.AttackDelay, target]],
      "petrified" + target,
      petrified.getDisplayName(),
      TransformationType.Override,
      (state, params) => {
        return hasTemporaryEffect(state, petrified.getId(), target)
          ? Infinity
          : undefined;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    petrified.getId(),
    CombatStat.AttackDelay,
  );
}
