import { valuesOf } from "../../../utils/CoreUtils";
import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { SpellElement } from "../../spells/Elements";
import {
  clearAutocastPointsCache,
  clearManaCostCache,
} from "../../spells/Spells";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";

class ShardDiligence extends Enhancement {
  getId(): string {
    return "shardDiligence";
  }
  getBaseName(): string {
    return t("Shard Diligence");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_OTHER;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 400,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 4,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Make your hired wizards (and yourself) more diligent and effective when transmuting elemental Shards.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t(
      "Elemental Shard Transmutation spells require -75%:mana: and -90%:wizardpower:",
    );
  }
}

export const shardDiligence = new ShardDiligence();

registerTransformation(
  [
    ...valuesOf(SpellElement).map((element) => [
      TransformationTags.ItemCost,
      Resource.Mana,
      "transmute-elementalShard" + element,
    ]),
  ],
  shardDiligence.getId(),
  shardDiligence.getBaseName(),
  TransformationType.Multiplier,
  (state) => (shardDiligence.isPurchased(state) ? 0.25 : 1.0),
);
registerTransformation(
  [
    ...valuesOf(SpellElement).map((element) => [
      TransformationTags.AutocastPointsConsumption,
      "transmute-elementalShard" + element,
    ]),
  ],
  shardDiligence.getId() + "autocast",
  shardDiligence.getBaseName(),
  TransformationType.Multiplier,
  (state) => (shardDiligence.isPurchased(state) ? 0.1 : 1.0),
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == shardDiligence) {
    state = clearAutocastPointsCache(state);
    state = clearManaCostCache(state);
  }
  return state;
}, shardDiligence.getId());
