import memoize from "sonic-memoize";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { cacheByTickCount } from "../Caching";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { Identifiable } from "../generic/Identifiable";
import {
  getResourceAmount,
  getResourceCap,
  grantResource,
  Resource,
} from "../Resources";
import {
  applyTransformations,
  explainTransformationsText,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { registerTimeTickListener } from "./TimeTick";

export type IncomeOverTimeCalculator = (
  state: GameState,
) => Partial<Record<Resource, number>>;

// If something produces (or consumes) income over time,
// register it here. This will make it visible in the UI later.
export class IncomeOverTimeProducer implements Identifiable {
  id: string;
  displayName: string;
  calculator: IncomeOverTimeCalculator;

  constructor(
    id: string,
    displayName: string,
    calculator: IncomeOverTimeCalculator,
  ) {
    this.id = id;
    this.displayName = displayName;
    this.calculator = calculator;
  }

  getId(): string {
    return this.id;
  }

  getDisplayName(): string {
    return this.displayName;
  }

  getCalculator(): IncomeOverTimeCalculator {
    return this.calculator;
  }
}

const IncomeOverTimeProducers = new DataStore<IncomeOverTimeProducer>();

// This method does not apply income if the resource is capped,
// which is a "smarter" behavior.
function onTick(state: GameState, elapsedTimeSec: number): GameState {
  const incomes = IncomeOverTimeProducers.getAll().map((producer) =>
    producer.getCalculator()(state),
  );

  let changed = false;
  const incomeSet = new Set(incomes);
  const appliedTotals: Partial<Record<Resource, number>> = {};
  const appliedIncomes = new Set<Partial<Record<Resource, number>>>();
  do {
    changed = false;
    for (let resString of Object.values(Resource)) {
      const resource = resString as Resource;
      if (
        getResourceAmount(state, resource) + (appliedTotals?.[resource] ?? 0) <
        getResourceCap(state, resource)
      ) {
        // Apply all incomes for this resource
        const incomesThatHaveResource = Array.from(incomeSet).filter(
          (income) => (income?.[resource] ?? 0) > 0,
        );
        incomesThatHaveResource.forEach((income) => {
          let canApply = true;
          for (let subResString in income) {
            const subResource = subResString as Resource;
            const subResourceIncome =
              (income[subResource] ?? 0) * elapsedTimeSec;
            if (
              subResourceIncome < 0 &&
              getResourceAmount(state, subResource) +
                (appliedTotals?.[subResource] ?? 0) <
                -subResourceIncome
            ) {
              canApply = false;
            }
          }
          if (canApply) {
            appliedIncomes.add(income);
            for (let subResString in income) {
              const subResource = subResString as Resource;
              appliedTotals[subResource] =
                (appliedTotals?.[subResource] ?? 0) +
                (income[subResource] ?? 0) * elapsedTimeSec;
            }
          }
        });
        if (appliedIncomes.size > 0) {
          appliedIncomes.forEach((appliedIncome) =>
            incomeSet.delete(appliedIncome),
          );
          appliedIncomes.clear();
          changed = true;
        }
      }
    }
  } while (changed && incomeSet.size > 0);

  // Now we have calculated the total incomes, grant the resources
  for (let resString in appliedTotals) {
    if ((appliedTotals?.[resString as Resource] ?? 0) != 0) {
      state = grantResource(
        resString as Resource,
        appliedTotals?.[resString as Resource] ?? 0,
      )(state);
    }
  }

  return state;
}

function calculatedIncomeCached(
  state: GameState,
  producerId: string,
  readCacheOnly: boolean,
): Partial<Record<Resource, number>> | null {
  let value = state.temp.incomeOverTimeCache[producerId];
  if (readCacheOnly) {
    return value;
  }
  if (value == null) {
    value = IncomeOverTimeProducers.getById(producerId).getCalculator()(state);
    state.temp.incomeOverTimeCache[producerId] = value;
  }
  return value;
}

export function clearCalculatedIncomeCache(state: GameState): GameState {
  state.temp.incomeOverTimeCache = {};
  return state;
}

function calculatedIncomesCached(
  state: GameState,
  readCacheOnly: boolean,
): { displayName: string; income: Partial<Record<Resource, number>> }[] {
  return cacheByTickCount(
    state,
    "incomePerSec",
    (state) =>
      IncomeOverTimeProducers.getAll().map((producer) => ({
        income: calculatedIncomeCached(state, producer.getId(), readCacheOnly),
        displayName: producer.getDisplayName(),
      })),
    readCacheOnly,
  );
}

const finalIncomeTags = memoize((resource) => [
  TransformationTags.FinalIncomePerSec,
  resource,
]);

// This method applies all incomes even if resources are capped.
function onTickApplyAll(state: GameState, elapsedTimeSec: number): GameState {
  if (elapsedTimeSec <= 0) {
    return state;
  }

  const incomes = calculatedIncomesCached(state, false);

  const appliedTotals: Partial<Record<Resource, number>> = {};
  for (let income of incomes) {
    let canApply = true;
    for (let subResString in income.income) {
      const subResource = subResString as Resource;
      const subResourceIncome =
        (income.income[subResource] ?? 0) * elapsedTimeSec;
      if (
        subResourceIncome < 0 &&
        getResourceAmount(state, subResource) < -subResourceIncome
      ) {
        canApply = false;
      }
    }

    if (canApply) {
      for (let subResString in income.income) {
        const subResource = subResString as Resource;
        const subResourceIncome =
          (income.income[subResource] ?? 0) * elapsedTimeSec;
        appliedTotals[subResource] =
          (appliedTotals?.[subResource] ?? 0) + subResourceIncome;
      }
    }
  }

  // Transform the totals
  for (let resource in appliedTotals) {
    appliedTotals[resource as Resource] =
      applyTransformations(
        finalIncomeTags(resource),
        state,
        (appliedTotals[resource as Resource] ?? 0) / elapsedTimeSec,
      ) * elapsedTimeSec;
  }

  // Now we have calculated the total incomes, grant the resources
  for (let resString in appliedTotals) {
    if ((appliedTotals?.[resString as Resource] ?? 0) != 0) {
      state = grantResource(
        resString as Resource,
        appliedTotals?.[resString as Resource] ?? 0,
      )(state);
    }
  }

  return state;
}

// Lower priority listener as otherwise mana bar doesn't fully fill
registerTimeTickListener("IncomeOverTime", onTickApplyAll, 10);

// Only use for display purposes
export function calculateIncomePerSecond(
  state: GameState,
): Partial<Record<Resource, number>> {
  return cacheByTickCount(state, "incomePerSecondDisplay", (state) => {
    let total: Partial<Record<Resource, number>> = {};
    const incomes = calculatedIncomesCached(state, true);
    incomes.forEach((income) => {
      let canApply = true;
      for (let subResString in income.income) {
        const subResource = subResString as Resource;
        const subResourceIncome = (income.income[subResource] ?? 0) * 0.5;
        if (
          subResourceIncome < 0 &&
          getResourceAmount(state, subResource) < -subResourceIncome
        ) {
          canApply = false;
        }
      }

      // Skip incomes that cannot be applied unless they are negative
      for (let resString in income.income) {
        const resource = resString as Resource;
        const existingIncome = total?.[resource] ?? 0;
        if (canApply || (income.income?.[resource] ?? 0) < 0) {
          total[resource] = existingIncome + (income.income?.[resource] ?? 0);
        }
      }
    });

    // Transform the totals
    for (let resource in total) {
      total[resource as Resource] = applyTransformations(
        finalIncomeTags(resource),
        state,
        total[resource as Resource] ?? 0,
      );
    }
    return total;
  });
}

export function explainIncomePerSecondPreTransform(
  state: GameState,
): Partial<Record<Resource, { displayName: string; income: number }[]>> {
  let total: Partial<
    Record<Resource, { displayName: string; income: number }[]>
  > = {};
  const incomes = calculatedIncomesCached(state, true);
  incomes.forEach((income) => {
    let canApply = true;
    let foundNonzero = false;
    for (let subResString in income.income) {
      const subResource = subResString as Resource;
      const subResourceIncome = (income.income[subResource] ?? 0) * 0.5;
      if (
        subResourceIncome < 0 &&
        getResourceAmount(state, subResource) < -subResourceIncome
      ) {
        canApply = false;
      }
      if (subResourceIncome != 0) {
        foundNonzero = true;
      }
    }

    if (foundNonzero) {
      for (let resString in income.income) {
        const resource = resString as Resource;
        const existingIncome = total?.[resource] ?? [];
        if (canApply || (income.income?.[resource] ?? 0) < 0) {
          existingIncome.push({
            income: income.income?.[resource],
            displayName: income.displayName,
          });
        }
        total[resource] = existingIncome;
      }
    }
  });

  return total;
}

export function explainIncomePerSecText(
  state: GameState,
): Partial<Record<Resource, string>> {
  const explanations = explainIncomePerSecondPreTransform(state);

  function toTextAddition(result: {
    displayName: string;
    income: number | undefined;
  }) {
    return t("{{amount}}/s ({{description}})", {
      amount:
        result.income > 0
          ? "+" + formatNumber(result.income)
          : formatNumber(result.income),
      description: result.displayName,
    });
  }

  const total = {};
  // Transform the totals
  for (let resource in explanations) {
    const values = explanations[resource] as {
      displayName: string;
      income: number;
    }[];
    const valuesText = values
      .map((value) => toTextAddition(value))
      .join(t(", "));

    const postTransformationExplanation = explainTransformationsText(
      finalIncomeTags(resource),
      state,
      total[resource as Resource] ?? 0,
    );

    if (postTransformationExplanation != "") {
      total[resource as Resource] = t(
        "{{valuesText}}; and further changed by: {{postTransformationExplanation}}",
        { valuesText, postTransformationExplanation },
      );
    } else {
      total[resource as Resource] = valuesText;
    }
  }
  return total;
}

export { IncomeOverTimeProducers };
