import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { getUnlockedElements, SpellElement } from "../../spells/Elements";
import { Enhancement } from "../Enhancement";
import { manaSteamers } from "./ManaSteamers";

class SewageSystem extends Enhancement {
  getId(): string {
    return "sewageSystemEnhancement";
  }
  getBaseName(): string {
    return t("Sewage System");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_PRODUCTION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 600,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 10,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [manaSteamers];
  }
  isVisible(state: GameState): boolean {
    return (
      super.isVisible(state) &&
      getUnlockedElements(state).includes(SpellElement.Poison)
    );
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Discover a new type of furniture, which converts :poisonessence: automatically into large amounts of :fireessence:, :earthessence:, :wateressence: and :airessence:.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t('Unlock furniture "Sewage System"');
  }
}

export const sewageSystemEnhancement = new SewageSystem();
