import { t } from "../../utils/I18n";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../action/ActionAreas";
import { Flag } from "../Flag";
import { hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import { SpellElement } from "../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Building, BuildingSpell } from "./Building";
import { getBuildingAmount } from "./Buildings";

class ResearcherDesk extends Building {
  getId(): string {
    return "researcherDesk";
  }

  getName(): string {
    return t("Researcher Desk");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A flat surface, perfect for the focused study of Magic. The spot for a hired Researcher!",
    );
  }

  getDisplayEffect(state: GameState): string {
    return t("+1 Researcher");
  }

  getBaseLandRequired(): number {
    return 4;
  }

  canTurnOff(): boolean {
    return false;
  }
}

export const researcherDesk = new ResearcherDesk();

class BuildResearcherDesk extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.OTHER] };
  }

  getBuilding(): Building {
    return researcherDesk;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Coins: 500,
      WaterEssence: 500,
      FireEssence: 150,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      Coins: 1.5,
      WaterEssence: 1.35,
      FireEssence: 1.35,
    };
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.ResearchersUnlocked) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }
}

export const buildResearcherDesk = new BuildResearcherDesk();

registerTransformation(
  [[TransformationTags.ResearcherAmount]],
  "researcherDesks",
  t("Researcher Desks"),
  TransformationType.Addition,
  (state, params) => getBuildingAmount(state, researcherDesk),
);
