import { useCallback, useMemo, useRef } from "react";
import { View } from "react-native";
import {
  assignWizardsToRule,
  estimateIntervalBetweenCastsForRule,
  estimateManaConsumptionPerSecPerRule,
  getAutocastRuleBySpell,
  getAutocastStatusByRule,
  getCurrentAutocastStrategy,
  getFreeWizards,
  toggleAutocastRuleDisabled,
} from "../../backend/autocast/Autocast";
import { Spells } from "../../backend/spells/Spells";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Markdown } from "./Markdown";
import { PlusMinusAllocator } from "./PlusMinusAllocator";
import Styles from "./Styles";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

export function SpellWizardsInfo(props: { spellId: string }) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const spell = Spells.getById(props.spellId);
  const rule = getAutocastRuleBySpell(state, spell);

  const ruleStatus = getAutocastStatusByRule(state, rule);
  const intervalBetweenCasts = estimateIntervalBetweenCastsForRule(
    state,
    rule,
    ruleStatus,
  );
  const manaCostPerSec = estimateManaConsumptionPerSecPerRule(
    state,
    rule,
    ruleStatus,
  );

  const helperText = useMemo(
    () =>
      ruleStatus.wizardsAssigned > 0 && !ruleStatus.disabled
        ? t(`*({{secs}}s/cast; {{mana}}:mana:/s)*`, {
            secs: formatNumber(intervalBetweenCasts, { showDecimals: true }),
            mana: formatNumber(manaCostPerSec, { showDecimals: true }),
          })
        : ruleStatus.disabled
        ? t("(Disabled)")
        : "",
    [
      intervalBetweenCasts,
      ruleStatus.wizardsAssigned,
      ruleStatus.disabled,
      manaCostPerSec,
    ],
  );

  const wizardsAssignedComponent = useMemo(
    () => (
      <Markdown>
        {t("Assign wizards {{helperText}}", {
          helperText,
        })}
      </Markdown>
    ),
    [helperText],
  );

  const onPressPlus = useCallback(
    () =>
      stateRef.current.apply((state) => {
        const rule = getAutocastRuleBySpell(state, spell);
        if (rule == null) {
          // We must create a rule
          const currentStrategy = getCurrentAutocastStrategy(state);
          const newRule = {
            id: Math.random().toString(),
            disabled: false,
            wizardsAssigned: 0,
            spellId: props.spellId,
            conditions: [],
          };
          currentStrategy.rules.push(newRule);
          state.global.autocast.currentStrategy = currentStrategy;
          state = assignWizardsToRule(state, newRule, 1);
        } else {
          state = assignWizardsToRule(state, rule, 1);
          if (rule.disabled) {
            state = toggleAutocastRuleDisabled(state, rule);
          }
        }
        return state;
      }),
    [props.spellId],
  );
  const onPressMinus = useCallback(() => {
    stateRef.current.apply((state) => {
      const rule = getAutocastRuleBySpell(state, spell);
      if (rule == null) {
        return state;
      }
      state = assignWizardsToRule(state, rule, -1);
      if (rule.disabled) {
        state = toggleAutocastRuleDisabled(state, rule);
      }
      if (getAutocastStatusByRule(state, rule).wizardsAssigned <= 0) {
        const currentStrategy = getCurrentAutocastStrategy(state);
        currentStrategy.rules = currentStrategy.rules.filter(
          (strategyRule) => rule != strategyRule,
        );
      }
      return state;
    });
  }, [props.spellId]);

  const allocator = useMemo(
    () => (
      <View style={[Styles.flexRowAlignCenter, Styles.ml8]}>
        <View style={Styles.mr4}>
          <Text>{ruleStatus.wizardsAssigned}</Text>
        </View>
        <PlusMinusAllocator
          disableMinus={ruleStatus.wizardsAssigned <= 0}
          disablePlus={getFreeWizards(state) <= 0}
          onPressPlus={onPressPlus}
          onPressMinus={onPressMinus}
        />
      </View>
    ),
    [
      ruleStatus.wizardsAssigned,
      getFreeWizards(state),
      onPressPlus,
      onPressMinus,
    ],
  );

  return (
    <View style={Styles.flexRowSpaceBetween}>
      <View style={Styles.flex}>{wizardsAssignedComponent}</View>
      {allocator}
    </View>
  );
}
