import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { clearCombatStatCache, CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { TemporaryEffect } from "../temporaryeffects/TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "../temporaryeffects/TemporaryEffects";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";

class RitualWeakness extends TemporaryEffect {
  getId(): string {
    return "ritualWeakness";
  }
  getDisplayName(): string {
    return t("Ritual Weakness");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "Your body is temporarily weak from starting a Ritual! -50%:attack:, :defense:, :dodge: and :critChance:",
    );
  }
  getIcon() {
    return require("./ritualWeakness.png");
  }

  isDispellable(): boolean {
    return false;
  }
  isCombatOnly(): boolean {
    return false;
  }
  isBeneficial(): boolean {
    return false;
  }
}

export const ritualWeakness = new RitualWeakness();

export function loadRitualWeakness() {
  TemporaryEffects.register(ritualWeakness);

  registerTransformation(
    [
      CombatStat.Attack,
      CombatStat.Defense,
      CombatStat.Dodge,
      CombatStat.Crit,
    ].map((stat) => [AttackTarget.Player, stat]),
    ritualWeakness.getId(),
    ritualWeakness.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(state, ritualWeakness.getId(), AttackTarget.Player)
        ? 0.5
        : 1.0,
  );
  registerTemporaryEffectListener(ritualWeakness.getId(), (state, effect) => {
    if (effect.id == ritualWeakness.getId()) {
      state = clearCombatStatCache(
        state,
        AttackTarget.Player,
        CombatStat.Attack,
      );
      state = clearCombatStatCache(
        state,
        AttackTarget.Player,
        CombatStat.Defense,
      );
      state = clearCombatStatCache(
        state,
        AttackTarget.Player,
        CombatStat.Dodge,
      );
      state = clearCombatStatCache(state, AttackTarget.Player, CombatStat.Crit);
    }
    return effect;
  });
}
