import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { SpellElement } from "../spells/Elements";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Airborne extends TemporaryEffect {
  getId(): string {
    return "airborne";
  }
  getDisplayName(): string {
    return t("Airborne");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t('-20:accuracy:; immune to Earth damage and to "Grounded"');
  }
}

export const airborne = new Airborne();

export function loadAirborne() {
  TemporaryEffects.register(airborne);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "airborneDamage",
    airborne.getDisplayName(),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(state, airborne.getId(), params.target) &&
        (params.tags ?? []).includes(SpellElement.Earth)
        ? 0
        : 1.0;
    },
  );

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.Accuracy, target]],
      "airborne" + target,
      airborne.getDisplayName(),
      TransformationType.Addition,
      (state, params) => {
        return hasTemporaryEffect(state, airborne.getId(), target) ? -20 : 0;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    airborne.getId(),
    CombatStat.Accuracy,
  );

  registerTemporaryEffectListener(
    "airborneGroundedPrevention",
    (state, temporaryEffect) => {
      if (
        !hasTemporaryEffect(state, airborne.getId(), temporaryEffect.target)
      ) {
        return temporaryEffect;
      }
      if (temporaryEffect.id != "grounded") {
        return temporaryEffect;
      }
      // Prevent!
      temporaryEffect.endTime = getSecondsPlayed(state) - 1.0;
      return temporaryEffect;
    },
  );
}
