import { LinearGradient } from "expo-linear-gradient";
import React, { useEffect, useMemo, useRef } from "react";
import { Animated, Platform, StyleSheet, View } from "react-native";
import { useSafeAreaInsets } from "react-native-safe-area-context";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
} from "../../backend/Resources";
import {
  estimateManaConsumptionPerSecPermaCast,
  getCurrentAutocastStrategy,
} from "../../backend/autocast/Autocast";
import { calculateIncomePerSecond } from "../../backend/timetick/IncomeOverTime";
import { formatNumber } from "../../utils/FormattingUtils";
import { useAuxOptions } from "../utility/AuxOptionsContext";
import { Text } from "../utility/Text";
import { GameIcon } from "../utility/icons/GameIcon";
import { useGameState } from "../utility/useGameState";

/*
 * This component displays a mana bar. However, it's a total hack and will
 * certainly not look ok if it's not full-screen-width.
 */
export default function ManaBar() {
  const auxOptions = useAuxOptions();
  const state = useGameState();
  const mana = getResourceAmount(state, Resource.Mana);
  const manaCap = getResourceCap(state, Resource.Mana);
  const insets = useSafeAreaInsets();
  const ratioFilled = Math.min(1.0, mana / manaCap);

  const filledAnimWithNativeDriver = useRef(
    new Animated.Value(ratioFilled),
  ).current;
  const filledAnimWithoutNativeDriver = useRef(
    new Animated.Value(ratioFilled),
  ).current;

  const manaIncome = calculateIncomePerSecond(state)?.Mana ?? 0;
  const permaCastCost = estimateManaConsumptionPerSecPermaCast(
    state,
    getCurrentAutocastStrategy(state),
  );

  const incomeText =
    manaIncome != 0
      ? " (" +
        (manaIncome > 0 ? "+" : "") +
        formatNumber(manaIncome, { showDecimals: true }) +
        "/s" +
        (permaCastCost != 0
          ? "; -" + formatNumber(permaCastCost, { showDecimals: true }) + "/s"
          : "") +
        ")"
      : "";

  useEffect(() => {
    if (state.options.doNotUseNativeDriver) {
      Animated.timing(filledAnimWithoutNativeDriver, {
        toValue: Math.max(ratioFilled, 1e-5),
        duration: 100,
        useNativeDriver: false,
      }).start();
    } else {
      Animated.timing(filledAnimWithNativeDriver, {
        toValue: Math.max(ratioFilled, 1e-5),
        duration: 100,
        useNativeDriver: true,
      }).start();
    }
  }, [ratioFilled]);
  const animatedStyle = useMemo(
    () => [
      styles.filled,
      {
        transform: [
          {
            scaleX: state.options.doNotUseNativeDriver
              ? filledAnimWithoutNativeDriver
              : filledAnimWithNativeDriver,
          },
        ],
      },
    ],
    [state.options.doNotUseNativeDriver],
  );
  const animatedComponent = useMemo(
    () => (
      <Animated.View
        key={state.options.doNotUseNativeDriver ? "yes" : "no"}
        style={[
          animatedStyle,
          insets.bottom < 10 && { backgroundColor: "#04c" },
        ]}
      >
        {insets.bottom >= 10 && (
          <LinearGradient
            colors={[`#04c`, `#04c0`]}
            style={[
              styles.gradient,
              {
                height: Math.max(
                  48,
                  (auxOptions.compactLayout ? 21 : 29) + insets.bottom,
                ),
              },
            ]}
          />
        )}
      </Animated.View>
    ),
    [
      state.options.doNotUseNativeDriver,
      animatedStyle,
      insets.bottom,
      auxOptions.compactLayout,
    ],
  );
  const incomeTextComponent = useMemo(
    () => <Text style={styles.income}>{incomeText}</Text>,
    [incomeText],
  );
  const manaTextComponent = useMemo(
    () => (
      <Text style={styles.textOverlay}>
        {formatNumber(mana)}/{formatNumber(manaCap)}
        {incomeTextComponent}
      </Text>
    ),
    [mana, manaCap, incomeTextComponent],
  );
  const accessibilityLabel = useMemo(
    () => `Mana: ${formatNumber(mana)}/${formatNumber(manaCap)} ${incomeText}`,
    [mana, manaCap, incomeText],
  );
  return useMemo(
    () => (
      <View
        style={[
          styles.container,
          { height: (auxOptions.compactLayout ? 21 : 29) + insets.bottom },
        ]}
      >
        {animatedComponent}
        <View>
          <View
            style={styles.row}
            accessible={true}
            accessibilityRole={Platform.OS !== "web" ? "header" : undefined}
            accessibilityLabel={accessibilityLabel}
          >
            <GameIcon icon="mana" style={styles.icon} />
            {manaTextComponent}
          </View>
          <View style={{ height: insets.bottom }} />
        </View>
      </View>
    ),
    [
      animatedComponent,
      accessibilityLabel,
      manaTextComponent,
      insets.bottom,
      auxOptions.compactLayout,
    ],
  );
}

const styles = StyleSheet.create({
  container: {
    alignItems: "center",
    flexDirection: "row",
    justifyContent: "center",
    width: "100%",
    backgroundColor: "#014",
    height: 29,
  },
  gradient: {
    width: "100%",
    position: "absolute",
    left: 0,
    top: 0,
  },
  filled: {
    position: "absolute",
    left: "-100%",
    top: 0,
    height: "100%",
    width: "200%",
    overflow: "hidden",
  },
  textOverlay: {
    color: "#fff",
    fontWeight: "bold",
  },
  income: {
    color: "#fff",
    fontWeight: "normal",
  },
  row: {
    flexDirection: "row",
    alignItems: "center",
  },
  icon: {
    height: 16,
    width: 16,
    marginRight: 4,
  },
  accessibilityHidden: {
    width: 1,
    height: 1,
    position: "absolute",
    left: 0,
    top: 0,
  },
});
