import memoize from "sonic-memoize";
import { fromEntries, keysOf } from "../../utils/CoreUtils";
import { cacheBySecondsPlayed } from "../Caching";
import { Flag } from "../Flag";
import { hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { registerTimeTickListener } from "../timetick/TimeTick";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { Action } from "./Action";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "./ActionAreas";

export const Actions = new DataStore<Action>();

export function getVisibleActionsForAreaBySubcategory(
  state: GameState,
  area: ActionArea,
): Partial<Record<ActionSubcategories, Action[]>> {
  return cacheBySecondsPlayed(
    state,
    "visibleActionsBySubcategory" + area,
    (state) => {
      const actionsBySubcategory = actionsByAreaAndSubcategory[area];
      return fromEntries(
        (area == ActionArea.HOME && state.options.hideChannelingSpellsOnHome
          ? keysOf(actionsBySubcategory).filter(
              (key) => key != ActionSubcategories.CHANNELING,
            )
          : keysOf(actionsBySubcategory)
        ).map((subcategory) => {
          return [
            subcategory,
            actionsBySubcategory[subcategory].filter((action) =>
              action.isVisible(state),
            ),
          ];
        }),
      );
    },
  );
}

const actionsByAreaAndSubcategory: Record<
  ActionArea,
  Partial<Record<ActionSubcategory, Action[]>>
> = {
  HOME: {},
  SPELL_MENU: {},
  STUDY: {},
  TRANSMUTE: {},
  EXPLORATION: {},
  ENHANCEMENT: {},
  BOOST: {},
  FAMILIARS: {},
};

export function registerAction(action: Action) {
  if (Actions.getByIdNullable(action.getId()) != null) {
    return;
  }
  Actions.register(action);
  const areas = action.getAreas();
  for (let area of keysOf(areas)) {
    const subcategories = areas[area];
    for (let subcategory of subcategories) {
      if (actionsByAreaAndSubcategory[area][subcategory] == null) {
        actionsByAreaAndSubcategory[area][subcategory] = [];
      }
      actionsByAreaAndSubcategory[area][subcategory].push(action);
    }
  }
}

export function getActionById(id: string) {
  return Actions.getById(id);
}

export function isAutoPurchaseUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.AutoPurchaseUnlocked);
}

export const getAutoPurchaseActions = memoize(() =>
  Actions.getAll().filter((action) => action.canAutoPurchaseMax()),
);

export function loadActionsCommonLogic() {
  registerTimeTickListener(
    "actionAutoPurchaseMax",
    (state, timeElapsedSec, isWarp) => {
      const secondsPlayed = getSecondsPlayed(state);
      if (
        Math.floor(state.temp.lastAutoPurchaseRun) != Math.floor(secondsPlayed)
      ) {
        getAutoPurchaseActions().forEach((action) => {
          if (state.run.autoPurchaseActions[action.getId()] ?? false) {
            if (action.isEnabled(state)) {
              state = action.doAction({ isAutomatic: true }, state);
            }
          }
          return state;
        });
        state.temp.lastAutoPurchaseRun = secondsPlayed;
      }
      return state;
    },
  );
}
