import memoize from "sonic-memoize";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import {
  modifyCompanionCurrentHP,
  modifyEnemyCurrentHP,
  modifyPlayerCurrentHP,
} from "../exploration/Exploration";
import { GameState } from "../GameState";
import { registerTimeTickListener } from "../timetick/TimeTick";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { applyTransformations } from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffects,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Poison extends TemporaryEffect {
  getId(): string {
    return "poison";
  }
  getDisplayName(): string {
    return t("Poison");
  }
  getIcon() {
    return require("./poison.png");
  }
  isBeneficial(): boolean {
    return false;
  }
  poisonDoTAmountTags = memoize((target) => [
    this.getId(),
    TransformationTags.PoisonDoTAmount,
    target,
  ]);
  getDamagePerSec(state: GameState, temporaryEffectData: TemporaryEffectData) {
    return applyTransformations(
      this.poisonDoTAmountTags(temporaryEffectData.target),
      state,
      temporaryEffectData.params.magnitude ?? 0,
      { target: temporaryEffectData.target },
    );
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("-{{amount}}:hp:/sec", {
      amount: formatNumber(this.getDamagePerSec(state, temporaryEffectData), {
        showDecimals: true,
      }),
    });
  }
  // For Poison, if the damage per sec is different, keep the one with the
  // higher total damage left
  mergeData(
    state: GameState,
    oldData: TemporaryEffectData,
    newData: TemporaryEffectData,
    mode: "add" | "replace",
  ): TemporaryEffectData {
    if (oldData.params.magnitude == newData.params.magnitude) {
      return super.mergeData(state, oldData, newData, mode);
    }
    const now = getSecondsPlayed(state);
    const totalDamageOld = (oldData.endTime - now) * oldData.params.magnitude;
    const totalDamageNew = (newData.endTime - now) * newData.params.magnitude;
    if (totalDamageNew < totalDamageOld) {
      return oldData;
    }
    return newData;
  }
}

export const poison = new Poison();

export function loadPoison() {
  TemporaryEffects.register(poison);

  registerTimeTickListener("PoisonDoT", (state, timeElapsedSec) => {
    const temporaryEffects = getTemporaryEffects(
      state,
      (state, effect) => effect.id == poison.getId(),
    );
    temporaryEffects.forEach((effect) => {
      if (effect.target === AttackTarget.Enemy) {
        state = modifyEnemyCurrentHP(
          state,
          -poison.getDamagePerSec(state, effect) * timeElapsedSec,
        );
      } else if (effect.target === AttackTarget.Familiar) {
        state = modifyCompanionCurrentHP(
          state,
          -poison.getDamagePerSec(state, effect) * timeElapsedSec,
          poison.getDisplayName(),
        );
      } else {
        state = modifyPlayerCurrentHP(
          state,
          -poison.getDamagePerSec(state, effect) * timeElapsedSec,
          poison.getDisplayName(),
        );
      }
    });
    return state;
  });
}
