import { getRandomElementFromArray } from "../../../utils/CoreUtils";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { getBuildingAmount } from "../../buildings/Buildings";
import { essenceCatalyst } from "../../buildings/catalysts/EssenceCatalyst";
import { monstiumCatalyst } from "../../buildings/catalysts/MonstiumCatalyst";
import { ManaSpout } from "../../buildings/mana/ManaSpout";
import { BuildSaleStall, SaleStall } from "../../buildings/SaleStall";
import { AttackTarget } from "../../exploration/AttackTarget";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import {
  getResourceAmount,
  getResourceCap,
  getResourceInfo,
  grantResource,
  Resource,
} from "../../Resources";
import { RESOURCE_FOR_ELEMENT } from "../../spells/channeling/ChannelingResources";
import {
  getUnlockedElements,
  grantElementExp,
  translateElement,
} from "../../spells/Elements";
import {
  getResearcherExpPerSecAndResearcher,
  getTotalResearchers,
  isStudyUnlocked,
} from "../../study/Study";
import { TemporaryEffect } from "../../temporaryeffects/TemporaryEffect";
import {
  getTemporaryEffectData,
  grantTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "../../temporaryeffects/TemporaryEffects";
import {
  clearCalculatedIncomeCache,
  IncomeOverTimeProducer,
  IncomeOverTimeProducers,
} from "../../timetick/IncomeOverTime";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { buildEvent } from "../EventMessageBuilder";
import { EventTag } from "../GameEvent";
import { loadBadChestEvent } from "./Chest";
import { registerRandomEventTrigger } from "./RandomEventTrigger";

class PoolOfManaTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "poolOfMana";
  }
  getDisplayName(): string {
    return t("Pool of Mana");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("+100%:mana:/sec from Mana Spouts");
  }
}

class EssenceCatalystResonanceTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "essenceCatalystResonance";
  }
  getDisplayName(): string {
    return t("Essence Catalyst Resonance");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("+25% Essence Catalyst effect");
  }
}

class SaleRushTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "saleRush";
  }
  getDisplayName(): string {
    return t("Sale Rush!");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Market Stalls work twice as fast and sell for +50% more :coins:");
  }
}

class ElementalGatheringTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "elementalGathering";
  }
  getDisplayName(): string {
    return t("Elemental Gathering");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    const resource = temporaryEffectData.params.resource;
    const amountPerSec = temporaryEffectData.params.amountPerSec;
    return t("+{{amount}}:{{resourceIcon}}:/sec", {
      amount: formatNumber(amountPerSec),
      resourceIcon: getResourceInfo(resource).icon,
    });
  }
}

class MonstiumCatalystResonanceTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "monstiumCatalystResonance";
  }
  getDisplayName(): string {
    return t("Monstium Catalyst Resonance");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("+25% Monstium Catalyst effect");
  }
}

const poolOfManaTemporaryEffect = new PoolOfManaTemporaryEffect();

const essenceCatalystResonanceTemporaryEffect =
  new EssenceCatalystResonanceTemporaryEffect();

const saleRushTemporaryEffect = new SaleRushTemporaryEffect();

const elementalGatheringTemporaryEffect =
  new ElementalGatheringTemporaryEffect();

const monstiumCatalystResonanceTemporaryEffect =
  new MonstiumCatalystResonanceTemporaryEffect();

export function loadRandomRepeatableEvents() {
  // Pool of Mana
  TemporaryEffects.register(poolOfManaTemporaryEffect);
  registerTransformation(
    [[TransformationTags.Production, Resource.Mana, ManaSpout.getId()]],
    poolOfManaTemporaryEffect.getId(),
    poolOfManaTemporaryEffect.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(state, poolOfManaTemporaryEffect.getId()) ? 2.0 : 1.0,
  );
  registerTemporaryEffectListener(
    poolOfManaTemporaryEffect.getId(),
    (state, effect) => {
      if (effect.id == poolOfManaTemporaryEffect.getId()) {
        state = clearCalculatedIncomeCache(state);
      }
      return effect;
    },
    true,
  );
  const manaSpoutEventBuilder = buildEvent(
    "manaSpoutEvent",
    t("Pool of Mana"),
    [EventTag.INCONSEQUENTIAL_RANDOM_EVENT],
  ).setOnTrigger(
    grantTemporaryEffect(poolOfManaTemporaryEffect.getId(), 300, {
      target: AttackTarget.Player,
    }),
  );
  manaSpoutEventBuilder.message(
    t(`You hear a sound coming from one of your Mana Spouts. When you investigate, you notice it is overflowing with Mana! It appears you've gotten lucky - they've found a pool of it!
  
**Your Mana Spouts will produce double the :mana: for the next 5 minutes!**`),
  );
  const manaSpoutEvent = manaSpoutEventBuilder.build();
  registerRandomEventTrigger(
    manaSpoutEvent,
    100,
    (state) => getBuildingAmount(state, ManaSpout) > 0,
  );

  // Essence Catalyst Resonance
  TemporaryEffects.register(essenceCatalystResonanceTemporaryEffect);
  registerTransformation(
    [[TransformationTags.CatalystEffectivity, essenceCatalyst.getId()]],
    essenceCatalystResonanceTemporaryEffect.getId(),
    essenceCatalystResonanceTemporaryEffect.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(state, essenceCatalystResonanceTemporaryEffect.getId())
        ? 1.25
        : 1.0,
  );
  const essenceCatalystEventBuilder = buildEvent(
    "essenceCatalystEvent",
    t("Essence Catalyst Resonance"),
    [EventTag.INCONSEQUENTIAL_RANDOM_EVENT],
  ).setOnTrigger(
    grantTemporaryEffect(essenceCatalystResonanceTemporaryEffect.getId(), 300, {
      target: AttackTarget.Player,
    }),
  );
  essenceCatalystEventBuilder.message(
    t(`Oddly, your Essence Catalysts begin to glow. Something is happening! They appear to be resonating with each other and becoming stronger. You wonder how long this will last...
  
**Your Essence Catalysts will be 25% more effective for the next 5 minutes!**`),
  );
  const essenceCatalystEvent = essenceCatalystEventBuilder.build();
  registerRandomEventTrigger(
    essenceCatalystEvent,
    100,
    (state) => getBuildingAmount(state, essenceCatalyst) > 0,
  );

  // Sale Rush!
  TemporaryEffects.register(saleRushTemporaryEffect);
  registerTransformation(
    [[TransformationTags.SaleStallRevenue]],
    saleRushTemporaryEffect.getId() + "1",
    saleRushTemporaryEffect.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(state, saleRushTemporaryEffect.getId()) ? 1.5 : 1.0,
  );
  registerTransformation(
    [[TransformationTags.TimePerAutoSalePerStall]],
    saleRushTemporaryEffect.getId() + "2",
    saleRushTemporaryEffect.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(state, saleRushTemporaryEffect.getId()) ? 0.5 : 1.0,
  );
  const saleRushEventBuilder = buildEvent("saleRushEvent", t("Sale Rush!"), [
    EventTag.INCONSEQUENTIAL_RANDOM_EVENT,
  ]).setOnTrigger(
    grantTemporaryEffect(saleRushTemporaryEffect.getId(), 300, {
      target: AttackTarget.Player,
    }),
  );
  saleRushEventBuilder.message(
    t(`The number of people coming to your home to buy items has suddenly become very high. It's a Sale Rush event! You decide to leverage this and make as much money as you can - while it lasts!
  
**Your Market Stalls will sell items twice as fast and earn 50% more :coins: per sale for the next 5 minutes!**`),
  );
  const saleRushEvent = saleRushEventBuilder.build();
  registerRandomEventTrigger(
    saleRushEvent,
    100,
    (state) => getBuildingAmount(state, SaleStall) > 0,
  );

  // Elemental Gathering
  TemporaryEffects.register(elementalGatheringTemporaryEffect);
  IncomeOverTimeProducers.register(
    new IncomeOverTimeProducer(
      "elementalGathering",
      t("Elemental Gathering"),
      (state) => {
        if (
          !hasTemporaryEffect(state, elementalGatheringTemporaryEffect.getId())
        ) {
          return {};
        }
        const income = {};
        const effectData = getTemporaryEffectData(
          state,
          elementalGatheringTemporaryEffect.getId(),
        );
        income[effectData.params.resource] = effectData.params.amountPerSec;
        return income;
      },
    ),
  );
  const elementalGatheringEventBuilder = buildEvent(
    "elementalGatheringEvent",
    t("Elemental Gathering"),
    [EventTag.INCONSEQUENTIAL_RANDOM_EVENT],
  ).setOnTrigger((state, eventParams) => {
    state = grantTemporaryEffect(
      elementalGatheringTemporaryEffect.getId(),
      300,
      {
        target: AttackTarget.Player,
        params: {
          resource: eventParams.resource,
          amountPerSec: eventParams.amountPerSec,
        },
      },
    )(state);
    return state;
  });
  elementalGatheringEventBuilder.message(
    t(`You stumble upon an incredible cache of :/essenceIcon/:. It'll take a little time, but there is so much of it that it'll fill your storages!
  
**You will obtain /essenceString/ for the next 5 minutes!**`),
  );
  const elementalGatheringEvent = elementalGatheringEventBuilder.build();
  registerRandomEventTrigger(
    elementalGatheringEvent,
    100,
    (state) => hasFlag(state, Flag.TransmutationUnlocked),
    (state) => {
      const element = getRandomElementFromArray(getUnlockedElements(state));
      const resource = RESOURCE_FOR_ELEMENT[element];
      const resourceCap = getResourceCap(state, resource);
      const amountPerSec = resourceCap / 10; // Fill in 10 seconds
      return {
        resource,
        amountPerSec,
        essenceIcon: getResourceInfo(resource).icon,
        essenceString: t("+{{amount}}:{{essenceIcon}}:/sec", {
          amount: formatNumber(amountPerSec),
          essenceIcon: getResourceInfo(resource).icon,
        }),
      };
    },
  );
  registerTemporaryEffectListener(
    elementalGatheringTemporaryEffect.getId(),
    (state, effect) => {
      if (effect.id == elementalGatheringTemporaryEffect.getId()) {
        state = clearCalculatedIncomeCache(state);
      }
      return effect;
    },
    true,
  );
  // Monstium Catalyst Resonance
  TemporaryEffects.register(monstiumCatalystResonanceTemporaryEffect);
  registerTransformation(
    [[TransformationTags.CatalystEffectivity, monstiumCatalyst.getId()]],
    monstiumCatalystResonanceTemporaryEffect.getId(),
    monstiumCatalystResonanceTemporaryEffect.getDisplayName(),
    TransformationType.Multiplier,
    (state) =>
      hasTemporaryEffect(
        state,
        monstiumCatalystResonanceTemporaryEffect.getId(),
      )
        ? 1.25
        : 1.0,
  );
  const monstiumCatalystEventBuilder = buildEvent(
    "monstiumCatalystEvent",
    t("Monstium Catalyst Resonance"),
    [EventTag.INCONSEQUENTIAL_RANDOM_EVENT],
  ).setOnTrigger(
    grantTemporaryEffect(
      monstiumCatalystResonanceTemporaryEffect.getId(),
      300,
      {
        target: AttackTarget.Player,
      },
    ),
  );
  monstiumCatalystEventBuilder.message(
    t(`Oddly, your Monstium Catalysts begin to glow purple. Are they resonating with each other? It might be a great time to Explore!
  
**Your Monstium Catalysts will be 25% more effective for the next 5 minutes!**`),
  );
  const monstiumCatalystEvent = monstiumCatalystEventBuilder.build();
  registerRandomEventTrigger(
    monstiumCatalystEvent,
    100,
    (state) => getBuildingAmount(state, monstiumCatalyst) > 0,
  );

  // Pile of Coins
  // Only triggers if max coins are less than 1M, for balance
  const pileOfCoinsEventBuilder = buildEvent(
    "pileOfCoinsEvent",
    t("Pile of Coins"),
    [EventTag.INCONSEQUENTIAL_RANDOM_EVENT],
  ).setOnTrigger((state, params) =>
    grantResource(Resource.Coins, params.amount)(state),
  );
  pileOfCoinsEventBuilder.message(
    t(`It's your lucky day! You stumble upon a large pile of :coins:Coins on the ground. You take them - they'll certainly come in handy!
    
**You have obtained /amountText/:coins:!**`),
  );
  const pileOfCoinsEvent = pileOfCoinsEventBuilder.build();
  registerRandomEventTrigger(
    pileOfCoinsEvent,
    100,
    (state) =>
      BuildSaleStall.isVisible(state) &&
      getResourceCap(state, Resource.Coins) < 1000000,
    (state) => {
      const amount =
        (Math.random() * 0.5 + 0.3) * getResourceCap(state, Resource.Coins);
      const amountText = formatNumber(amount);
      return {
        amount,
        amountText,
      };
    },
  );

  const elementBookEventBuilder = buildEvent(
    "elementBookEvent",
    t("An Offer of /elementText/"),
    [EventTag.INCONSEQUENTIAL_RANDOM_EVENT],
  );
  elementBookEventBuilder
    .message(
      t(`You stumble upon a traveling merchant who demands your attention!

"Hello there! You look like a wizard of some renown. I have a deal for you! I have this "Book of /elementText/" that I've come across, and it seems like it could be *perfect* for you! You're certain to get a nice proficiency boost with it - it's worth every coin! What do you say? Interested?"
    
You ponder whether the steep price might be worthwhile...`),
    )
    .option(
      t("Buy the Book (/coinAmountText/ Coins)"),
      {
        transform(state, params) {
          state = grantResource(Resource.Coins, -params.coinAmount)(state);
          state = grantElementExp(params.element, params.exp)(state);
          return state;
        },
        isEnabled: (state, params) =>
          getResourceAmount(state, Resource.Coins) >= params.coinAmount,
      },
      "purchased",
    )
    .option(t("Decline"), "declined")
    .option(t("Maybe later"))
    .end();
  elementBookEventBuilder
    .message(
      t(`You decide to buy the book. The merchant smiles while taking your :coins:. "Nice doing business with you," he says, then hands over the book. He walks away soon afterward.
  
You flip through the book. It has information about /elementText/ that you had no idea of! You feel like you might have made a good purchase.

**You have obtained /expText/ /elementText/ exp!**`),
    )
    .tag("purchased")
    .end();
  elementBookEventBuilder
    .message(
      t(
        `You decide not to buy the book. The merchant nods. "I understand. See you next time!" Casually waving at you, he walks away.`,
      ),
    )
    .tag("declined")
    .end();
  const elementBookEvent = elementBookEventBuilder.build();
  registerRandomEventTrigger(
    elementBookEvent,
    100,
    (state) => isStudyUnlocked(state),
    (state) => {
      const element = getRandomElementFromArray(getUnlockedElements(state));

      // Exp gained is equal to the one generated by SQRT(researchers) in element for 30 in-game minutes
      // Example: At base rates and 4 researchers, it will be 1 researcher hour of exp: 10800 Exp
      // (non - Primary, brings Lv1 -> Lv7)
      // Late game, with 64 researchers, it will be 4 researcher hours of exp, or about a quarter of the
      // impact of the early game one
      const exp =
        Math.sqrt(getTotalResearchers(state)) *
        getResearcherExpPerSecAndResearcher(state)[element] *
        60 *
        30;
      const coinAmount =
        getResourceCap(state, Resource.Coins) * (Math.random() * 0.45 + 0.5);
      const elementText = translateElement(element);
      const expText = formatNumber(exp);
      const coinAmountText = formatNumber(coinAmount);
      return {
        element,
        exp,
        coinAmount,
        elementText,
        expText,
        coinAmountText,
      };
    },
  );

  loadBadChestEvent();
}
