import React, { ReactNode } from "react";
import { Card as PaperCard, CardProps, useTheme } from "react-native-paper";

export function Card(
  props: Omit<CardProps, "elevation"> & { children?: ReactNode },
) {
  const theme = useTheme();
  return (
    <PaperCard
      {...props}
      mode="outlined"
      // elevation={0}
      style={[{ backgroundColor: theme.colors.background }, props?.style]}
    >
      {props.children}
    </PaperCard>
  );
}
