import { LevelUpListeners } from "../events/progression/LevelUp";
import { registerRetirementListener } from "../Retire";
import { storylineHintUnlockChecker, Storylines } from "./Storylines";

function loadStorylinesCore(): void {
  registerRetirementListener("StorylineBonusActivator", (state, isMock) => {
    Storylines.getAll().forEach((storyline) => {
      if (storyline.isCompleted(state) && !storyline.isBonusActive(state)) {
        state = storyline.activateBonus(state);
      }
    });
    return state;
  });

  LevelUpListeners.register(
    storylineHintUnlockChecker,
    "storylineHintUnlocker",
  );
}

export { loadStorylinesCore };
