import { t } from "../../utils/I18n";
import { getBuildingAmountTurnedOn } from "../buildings/Buildings";
import { getSaleStallPriceMultiplier, SaleStall } from "../buildings/SaleStall";
import { GameState } from "../GameState";
import { getAmountOfItem } from "../items/Inventory";
import { ItemOccurrence } from "../items/Item";
import { Items } from "../items/Items";
import { getResourceAmount, getResourceCap, Resource } from "../Resources";
import { registerTimeTickListener } from "../timetick/TimeTick";
import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";

function autosellOneItem(state: GameState): GameState {
  const autosellItemOccurrences = state.run.saleStallItems;
  const currentlyInInventory = autosellItemOccurrences.filter(
    (itemOccurrence) => getAmountOfItem(itemOccurrence, state) > 0,
  );
  const distinctItemCount = currentlyInInventory.length;
  if (distinctItemCount > 0) {
    const selectedItem =
      currentlyInInventory[Math.floor(Math.random() * distinctItemCount)];
    const item = Items.getById(selectedItem.itemId);
    const sellAction = item.getSellOneAction(state, selectedItem.params, [
      TransformationTags.FromSaleStalls,
    ]);
    state = sellAction?.transform?.(state) ?? state;
  }
  return state;
}

export function isItemBeingAutosold(state: GameState, item: ItemOccurrence) {
  return (
    state.run.saleStallItems.find(
      (i) =>
        i.itemId == item.itemId &&
        JSON.stringify(i.params) == JSON.stringify(item.params),
    ) != null
  );
}

export function toggleItemAutosold(state: GameState, item: ItemOccurrence) {
  if (isItemBeingAutosold(state, item)) {
    state.run.saleStallItems = state.run.saleStallItems.filter(
      (i) =>
        i.itemId != item.itemId ||
        JSON.stringify(i.params) != JSON.stringify(item.params),
    );
  } else {
    state.run.saleStallItems.push(item);
  }
  return state;
}

export function disableAllAutosold(state: GameState) {
  state.run.saleStallItems = [];
  return state;
}

export function loadMarket() {
  registerTransformation(
    [[TransformationTags.ItemSalePrice, TransformationTags.FromSaleStalls]],
    "saleStallRevenue",
    t("From Market Stalls"),
    TransformationType.Multiplier,
    (state) => getSaleStallPriceMultiplier(state),
  );
  const tags = [TransformationTags.TimePerAutoSalePerStall];
  registerTimeTickListener("Autosale", (state, timeElapsedSec, isWarp) => {
    const timePerSale = applyTransformations(tags, state, 60);
    const saleStalls = getBuildingAmountTurnedOn(state, SaleStall);
    if (saleStalls > 0) {
      const previousProgress = state.run.saleStallProgressRatio;
      const additionalProgress = (saleStalls * timeElapsedSec) / timePerSale;
      let totalProgress = previousProgress + additionalProgress;
      while (
        totalProgress >= 1.0 &&
        getResourceAmount(state, Resource.Coins) <
          getResourceCap(state, Resource.Coins)
      ) {
        totalProgress -= 1.0;
        state = autosellOneItem(state);
      }
      state.run.saleStallProgressRatio = Math.min(1.0, totalProgress);
    }

    return state;
  });
}
