const SWIPE_EDGE_WIDTH_DEFAULT = 128;
const SWIPE_EDGE_WIDTH_LEFT_WITH_BOTTOM_MENU = 40;

function BottomSafeAreaPadding() {
  const safeAreaInsets = useSafeAreaInsets();
  const theme = useTheme();
  if (Platform.OS != "ios" && Platform.OS != "android") {
    return null;
  }
  return (
    <View
      style={{
        backgroundColor: theme.colors.surface,
        height: safeAreaInsets.bottom,
      }}
    />
  );
}

function IntroContents() {
  const theme = useTheme();
  const headerContainerStyle = useMemo(
    () => ({
      borderBottomColor: theme.colors.outlineVariant,
      borderBottomWidth: 2,
    }),
    [theme],
  );
  const centerComponent = useMemo(
    () => (
      <View style={styles.centerText}>
        <Text
          accessibilityRole="header"
          style={{ color: theme.colors.primary }}
        >
          {t("Magic Research 2")}
        </Text>
      </View>
    ),
    [theme],
  );
  return (
    <View style={styles.maxContainer} nativeID={theme.dark ? "dark" : "light"}>
      <OrientationLockManager />
      <View style={styles.safeAreaContainer}>
        <Appbar.Header style={headerContainerStyle}>
          {centerComponent}
        </Appbar.Header>
        <View
          style={[
            styles.biggerContainer,
            { backgroundColor: theme.colors.background },
          ]}
        >
          <View style={styles.introContainer}>
            <ScrollView>
              <Intro />
            </ScrollView>
            <BottomSafeAreaPadding />
          </View>
        </View>
      </View>
    </View>
  );
}

type MainAppContentsProps = {
  setPlayerState: any;
  onLoadingProgress: any;
};

function resetKeys(pinnedSectionObjects: { section: string; key: number }[]) {
  for (let idx in pinnedSectionObjects) {
    pinnedSectionObjects[idx].key = Number.parseInt(idx) + 1;
  }
  return pinnedSectionObjects;
}

function MainAppContents(props: MainAppContentsProps) {
  const { height, width } = useWindowDimensions();
  const auxOptions = useAuxOptions();
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const uiContext = useContext(UIContext);

  const [menuShown, setMenuShown] = useState(false);
  const [rightMenuShown, setRightMenuShown] = useState("none");
  const layout = getLayoutType(state, height, width);

  const allSections = getAllSections();
  const sectionData = Object.fromEntries(
    allSections.map((section) => [section.id, section]),
  );

  const [section, setSection] = useState("Home");
  const [pinnedSectionObjects, setPinnedSectionObjects] = useState<
    { section: string; key: number }[]
  >([]);
  const pinnedSectionObjectsRef = useRef(pinnedSectionObjects);
  pinnedSectionObjectsRef.current = pinnedSectionObjects;

  const activeSections = [
    section,
    ...pinnedSectionObjects.map((pinnedSection) => pinnedSection.section),
  ];
  const [_uiActiveSections, setUiActiveSections] = useUIStateFromContext(
    uiContext,
    "active_sections",
    activeSections,
  );

  useEffect(() => {
    setUiActiveSections(activeSections);
  }, [JSON.stringify(activeSections)]);

  useEffect(() => {
    if (layout.activeContentTabs == 1) {
      setPinnedSectionObjects([]);
    }
    const newSectionObjects = pinnedSectionObjectsRef.current.slice(
      0,
      layout.activeContentTabs - 1,
    );
    setPinnedSectionObjects(resetKeys(newSectionObjects));
  }, [layout.activeContentTabs]);

  const togglePinnedSection = useCallback(
    (section: string) => {
      // If the section is already there, remove it
      if (pinnedSectionObjectsRef.current.find((s) => s.section == section)) {
        setPinnedSectionObjects(
          pinnedSectionObjectsRef.current.filter((s) => s.section != section),
        );
        return;
      }

      // Otherwise, choose the smallest possible index, and push it to the end
      // Obtain the lowest free key, so that we are constantly reusing numbers
      // so that we don't need to re-load things
      const usedIndices = pinnedSectionObjectsRef.current.map((s) => s.key);
      let lowestFreeIndex = 1;
      while (usedIndices.includes(lowestFreeIndex)) {
        lowestFreeIndex++;
      }
      const newPinnedSectionObjects = [
        ...pinnedSectionObjectsRef.current,
        { section: section, key: lowestFreeIndex },
      ];
      setPinnedSectionObjects(
        newPinnedSectionObjects.slice(0, layout.activeContentTabs - 1),
      );

      // Finally, if the current selected section is the one that was pinned,
      // we want to reset it to something new
    },
    [setPinnedSectionObjects, JSON.stringify(layout)],
  );

  const theme = useTheme();

  const [endingHistoryIndex, setEndingHistoryIndex] = useState(null);

  const processUICommand = useCallback(
    (uiCommand: string) => {
      if (
        uiCommand == "goToExploration" &&
        !activeSections.includes("Exploration")
      ) {
        setSection("Exploration");
      } else if (
        uiCommand == "goToEnding" &&
        !activeSections.includes("Ending")
      ) {
        setSection("Ending");
        setPinnedSectionObjects([]);
      } else if (uiCommand == "goToDebug") {
        setSection("Debug");
      } else if (uiCommand == "closeAllMenus") {
        setMenuShown(false);
        setRightMenuShown("none");
      } else if (uiCommand.startsWith("endingHistoryOverride:")) {
        setEndingHistoryIndex(uiCommand.split(":")[1]);
      }
    },
    [
      setSection,
      activeSections,
      setPinnedSectionObjects,
      setEndingHistoryIndex,
    ],
  );

  const processUICommands = useCallback(
    (state: GameState) => {
      const uiCommands = getUICommands(state);
      uiCommands.forEach(processUICommand);
      state.run.uiCommands = uiCommands.filter(
        (uiCommand) =>
          uiCommand != "goToExploration" &&
          uiCommand != "goToEnding" &&
          uiCommand != "closeAllMenus" &&
          !uiCommand.startsWith("endingHistoryOverride:") &&
          uiCommand != "goToDebug",
      );
      return state;
    },
    [processUICommand],
  );

  useEffect(() => {
    addGameStateListener("appUiCommand", (newState) => {
      const uiCommands = getUICommands(newState);
      if (
        uiCommands.filter(
          (command) =>
            [
              "goToExploration",
              "goToEnding",
              "closeAllMenus",
              "goToDebug",
            ].includes(command) || command.startsWith("endingHistoryOverride:"),
        ).length > 0
      ) {
        return (state) => processUICommands(state);
      }
    });
    return () => clearGameStateListener("appUiCommand");
  }, [processUICommands]);

  const onMenuPress = useCallback(() => {
    setMenuShown(!menuShown);
    setRightMenuShown("none");
  }, [menuShown]);
  const onMenuOpen = useCallback(() => {
    setMenuShown(true);
    setRightMenuShown("none");
  }, []);
  const onMenuClose = useCallback(() => {
    setMenuShown(false);
  }, []);
  const onEventMenuPress = useCallback(() => {
    if (rightMenuShown !== "event") {
      setRightMenuShown("event");
    } else {
      setRightMenuShown("none");
    }
    setMenuShown(false);
  }, [rightMenuShown]);
  const onEventMenuOpen = useCallback(() => {
    setMenuShown(false);
    setRightMenuShown("event");
  }, []);
  const onSpellMenuOpen = useCallback(() => {
    setMenuShown(false);
    setRightMenuShown("spell");
  }, []);
  const onRightMenuClose = useCallback(() => {
    setMenuShown(false);
    setRightMenuShown("none");
  }, []);
  const onSpellMenuPress = useCallback(() => {
    if (rightMenuShown !== "spell") {
      setRightMenuShown("spell");
    } else {
      setRightMenuShown("none");
    }
    setMenuShown(false);
  }, [rightMenuShown]);
  const onNavigationPress = useCallback(
    (section: string) => {
      setSection(section);
      setMenuShown(false);
      setRightMenuShown("none");
    },
    [setSection],
  );

  const ongoingEventCount = Object.values(state.run.eventOccurrences).filter(
    (event) => {
      return (
        event.currentMessageId &&
        !getEventMessageById(event.currentMessageId).isTerminal() &&
        getEventMessageById(event.currentMessageId).isActionable(
          state,
          event.params,
        )
      );
    },
  ).length;

  useEffect(() => {
    setSection("Home");
    setPinnedSectionObjects([]);
  }, [state.world.totalTimesResetted]);

  const showBottomResourceBar =
    !layout.wideLayout &&
    !sectionData[activeSections[0]]?.hideBottomResourceBar &&
    !state.options.hideBottomResourceViewer &&
    keysOf(state.run.resources).length > 1;

  const centerComponent = useMemo(
    () => (
      <View style={styles.centerText}>
        <Text
          accessibilityRole="header"
          style={{ color: theme.colors.primary }}
        >
          {activeSections.length == 1
            ? activeSections[0]
            : t("Magic Research 2")}
        </Text>
      </View>
    ),
    [activeSections.join(), theme],
  );

  const scrollRefs = Object.fromEntries(
    allSections.map((section) => [section.id, useRef<ScrollView>()]),
  );
  scrollRefs["Main"] = useRef<ScrollView>();

  const [scrollPositions, setScrollPositions] = useUIStateFromContext<
    Record<string, number>
  >(uiContext, "mainScrollPositions", {});
  const onScrollPerSection = useMemo(() => {
    return Object.fromEntries(
      allSections.map((sectionData) => [
        sectionData.id,
        (event: any) => {
          const scrollValue = event.nativeEvent.contentOffset.y;
          scrollPositions[sectionData.id] = scrollValue;
          // console.warn(
          //   "setting " +
          //     sectionData.id +
          //     " to " +
          //     scrollPositions?.[sectionData.id],
          // );
          setScrollPositions(scrollPositions);
        },
      ]),
    );
  }, []);
  useEffect(() => {
    for (let section of activeSections) {
      if (!sectionData[section]?.scrollable) {
        return;
      }
      const shouldScroll = Platform.select({ default: true, web: false });
      if (!shouldScroll) {
        return;
      }
      // console.warn("scrolling" + section + " to " + scrollPositions?.[section]);
      if (sectionData[section].scrollView) {
        scrollRefs["Main"].current.scrollTo({
          y: scrollPositions?.[section] ?? 0,
          animated: false,
        });
      } else {
        scrollRefs[section]?.current?.scrollTo?.({
          y: scrollPositions?.[section] ?? 0,
          animated: false,
        });
      }
    }
  }, [activeSections.join(",")]);

  const showBottomMenu = state.options.enableBottomMenu;
  const leftHandedMode = state.options.leftHandedMode;
  const putCastSpellOnBottomMenu = state.options.putCastSpellOnBottomMenu;

  function wrapInSection(
    component: React.ReactElement,
    sectionTitle: string,
    activeSection: string,
    keepInMemory: boolean,
    insets: { top?: number; bottom?: number },
    fullScreen?: boolean,
    backgroundColor?: string,
  ) {
    let contents = null;
    if (!keepInMemory) {
      contents =
        activeSection != sectionTitle ? (
          <View key={sectionTitle} />
        ) : (
          <View key={sectionTitle}>{component}</View>
        );
    } else {
      contents = (
        <View
          key={sectionTitle}
          style={activeSection != sectionTitle && { display: "none" }}
        >
          <GameStateFreezer isVisible={activeSection == sectionTitle}>
            {component}
          </GameStateFreezer>
        </View>
      );
    }

    if (fullScreen) {
      return activeSection == sectionTitle ? (
        <Portal key={sectionTitle}>
          <View
            style={[
              styles.fullscreenContainer,
              {
                backgroundColor,
                paddingTop: insets?.top,
                paddingBottom: insets?.bottom,
              },
            ]}
          >
            <ScrollView style={styles.fullscreenContainerInside}>
              {contents}
            </ScrollView>
          </View>
        </Portal>
      ) : null;
    } else {
      return contents;
    }
  }

  const onEndingClose = useCallback(() => {
    if (endingHistoryIndex != null) {
      setEndingHistoryIndex(null);
      setSection("Stats");
    } else {
      setSection("Home");
    }
    stateRef.current.apply((state) => {
      for (let listener of EndingCloseListeners.getAll()) {
        state = listener(state);
      }
      return state;
    });
  }, [endingHistoryIndex]);

  const sectionIndices = useMemo(() => {
    const indices: number[] = [];
    for (let i = 0; i < layout.activeContentTabs; i++) {
      indices.push(i);
    }
    return indices;
  }, [layout.activeContentTabs]);

  const safeAreaInsets = useSafeAreaInsets();

  const mainSections = useMemo(() => {
    let unusedIndex = 1;
    return (
      <View
        accessibilityElementsHidden={menuShown || rightMenuShown != "none"}
        importantForAccessibility={
          menuShown || rightMenuShown != "none" ? "no-hide-descendants" : "auto"
        }
        style={{
          flex: 1,
          flexDirection: "row",
          alignItems: "stretch",
          justifyContent: "space-evenly",
        }}
      >
        {sectionIndices.map((index) => {
          if (layout.activeContentTabs == 1 && index > 0) {
            // Optimization for phones and similar
            return null;
          }
          const currentSection =
            index == 0
              ? section
              : pinnedSectionObjects[index - 1]?.section ?? "-";
          let sectionKey =
            index == 0 ? 0 : pinnedSectionObjects?.[index - 1]?.key;
          if (sectionKey === undefined) {
            while (pinnedSectionObjects.find((s) => s.key == unusedIndex)) {
              unusedIndex++;
            }
            sectionKey = unusedIndex;
            unusedIndex++;
          }
          const showHeader = pinnedSectionObjects.length > 0;

          const scrollViewContents = (
            <ScrollView
              //@ts-ignore Not sure why this is giving error
              ref={scrollRefs["Main"]}
              style={styles.mainContainer}
              onScroll={onScrollPerSection[currentSection]}
              contentContainerStyle={
                currentSection != "Inventory" &&
                currentSection != "Transmute" &&
                styles.container
              }
            >
              {wrapInSection(
                <Study />,
                "Study",
                currentSection,
                sectionData["Study"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Inventory />,
                "Inventory",
                currentSection,
                sectionData["Inventory"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Transmute />,
                "Transmute",
                currentSection,
                sectionData["Transmute"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Enhance />,
                "Enhance",
                currentSection,
                sectionData["Enhance"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Boosts />,
                "Boosts",
                currentSection,
                sectionData["Boosts"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <FamiliarsComponent />,
                "Familiars",
                currentSection,
                sectionData["Familiars"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <SpellcraftsComponent />,
                "Spellcraft",
                currentSection,
                sectionData["Spellcraft"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <RitualsComponent />,
                "Rituals",
                currentSection,
                sectionData["Rituals"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Ending
                  close={onEndingClose}
                  overrideEnding={
                    endingHistoryIndex != null
                      ? state.global.endingsHistory[endingHistoryIndex]
                      : undefined
                  }
                />,
                "Ending",
                currentSection,
                sectionData["Ending"]?.keepInMemory,
                safeAreaInsets,
                sectionData["Ending"]?.fullScreen,
                theme.colors.background,
              )}
              {wrapInSection(
                <Worlds />,
                "Worlds",
                currentSection,
                sectionData["Worlds"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Retirement />,
                "Retirement",
                currentSection,
                sectionData["Retirement"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Compendium />,
                "Compendium",
                currentSection,
                sectionData["Compendium"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Stats />,
                "Stats",
                currentSection,
                sectionData["Stats"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Options />,
                "Options",
                currentSection,
                sectionData["Options"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <Help />,
                "Help",
                currentSection,
                sectionData["Help"]?.keepInMemory,
                safeAreaInsets,
              )}
              {wrapInSection(
                <About />,
                "About",
                currentSection,
                sectionData["About"]?.keepInMemory,
                safeAreaInsets,
              )}
              {currentSection === "Debug" && <Debug />}
            </ScrollView>
          );
          const explorationContents = (
            <View style={styles.mainContainer}>
              <GameStateFreezer isVisible={currentSection == "Exploration"}>
                <Exploration />
              </GameStateFreezer>
            </View>
          );
          const synchroContents = (
            <View style={styles.mainContainer}>
              <GameStateFreezer isVisible={currentSection == "Synchro"}>
                <Synchro />
              </GameStateFreezer>
            </View>
          );
          return (
            <VisibilityContext.Provider
              key={sectionKey ?? index}
              value={index < layout.activeContentTabs}
            >
              <View
                key={sectionKey ?? index}
                style={[
                  showHeader && {
                    marginHorizontal: 4,
                    borderLeftWidth: 1,
                    borderRightWidth: 1,
                    borderColor: theme.colors.outlineVariant,
                  },
                  {
                    flex: 1,
                    display: currentSection == "-" ? "none" : undefined,
                  },
                ]}
              >
                {showHeader && (
                  <View
                    style={[
                      {
                        height: 56,
                        paddingVertical: 8,
                        paddingHorizontal: 16,
                        flexDirection: "row",
                        alignItems: "center",
                        borderBottomWidth: 1,
                        borderBottomColor: theme.colors.outlineVariant,
                      },
                    ]}
                  >
                    <View style={{ flex: 1 }}>
                      <UtilityText h2>{currentSection}</UtilityText>
                    </View>
                    {index > 0 && (
                      <View
                        style={{
                          flexDirection: "row",
                          alignItems: "center",
                        }}
                      >
                        <MaterialIcons
                          name="close"
                          size={20}
                          color={theme.colors.onBackground}
                          onPress={() => togglePinnedSection(currentSection)}
                          style={{ marginLeft: 8 }}
                          accessibilityRole="button"
                          accessibilityLabel={"Close Section"}
                        />
                      </View>
                    )}
                  </View>
                )}
                <View
                  style={[
                    { flex: 1 },
                    !sectionData[currentSection]?.scrollView && {
                      display: "none",
                    },
                  ]}
                >
                  {scrollViewContents}
                </View>
                <View
                  style={[
                    styles.mainContainer,
                    currentSection !== "Home" && { display: "none" },
                  ]}
                >
                  <GameStateFreezer isVisible={currentSection == "Home"}>
                    <Home
                      onScroll={onScrollPerSection["Home"]}
                      scrollRef={scrollRefs["Home"]}
                    />
                  </GameStateFreezer>
                </View>
                <View
                  style={[
                    styles.mainContainer,
                    currentSection !== "Wizards" && { display: "none" },
                  ]}
                >
                  <GameStateFreezer isVisible={currentSection == "Wizards"}>
                    <Wizards />
                  </GameStateFreezer>
                </View>
                <View
                  style={[
                    styles.mainContainer,
                    currentSection !== "Exploration" && { display: "none" },
                  ]}
                >
                  {explorationContents}
                </View>
                <View
                  style={[
                    styles.mainContainer,
                    currentSection !== "Synchro" && { display: "none" },
                  ]}
                >
                  {synchroContents}
                </View>
              </View>
            </VisibilityContext.Provider>
          );
        })}
      </View>
    );
  }, [
    theme,
    menuShown,
    rightMenuShown,
    pinnedSectionObjects,
    sectionIndices,
    section,
    togglePinnedSection,
    endingHistoryIndex,
    safeAreaInsets,
  ]);

  const backCallback = useCallback(() => {
    if (menuShown) {
      setMenuShown(false);
      return true;
    }
    if (rightMenuShown != "none") {
      setRightMenuShown("none");
      return true;
    }
    return false;
  }, [menuShown, rightMenuShown, setMenuShown, setRightMenuShown]);

  useEffect(() => {
    const handler = BackHandler.addEventListener(
      "hardwareBackPress",
      backCallback,
    );

    return () => handler.remove();
  }, [backCallback]);

  const contentsWide = useMemo(
    () => (
      <View
        style={[
          styles.biggerContainer,
          { backgroundColor: theme.colors.background },
        ]}
        onAccessibilityEscape={
          menuShown || rightMenuShown != "none" ? backCallback : undefined
        }
      >
        <View style={styles.horizontalContainer}>
          <Drawer
            drawerPosition="right"
            drawerType={layout.showEventsMenu ? "permanent" : "front"}
            open={rightMenuShown == "event"}
            onOpen={onEventMenuOpen}
            onClose={onRightMenuClose}
            swipeEnabled={rightMenuShown == "event"}
            swipeEdgeWidth={SWIPE_EDGE_WIDTH_DEFAULT}
            renderDrawerContent={() => (
              <GameStateFreezer
                isVisible={layout.showEventsMenu || rightMenuShown === "event"}
              >
                <EventList />
              </GameStateFreezer>
            )}
          >
            <Drawer
              drawerPosition="right"
              drawerType={layout.showSpellMenu ? "permanent" : "front"}
              open={rightMenuShown == "spell"}
              onOpen={onSpellMenuOpen}
              onClose={onRightMenuClose}
              swipeEdgeWidth={SWIPE_EDGE_WIDTH_DEFAULT}
              renderDrawerContent={() => (
                <GameStateFreezer
                  isVisible={layout.showSpellMenu || rightMenuShown === "spell"}
                >
                  <SpellMenu favoritesOnBottom={false} />
                </GameStateFreezer>
              )}
            >
              <Drawer
                open={true}
                drawerType="permanent"
                onOpen={onMenuOpen}
                onClose={onMenuClose}
                renderDrawerContent={() => (
                  <MenuContents
                    visible={true}
                    wide={true}
                    pinnedSections={pinnedSectionObjects.map((s) => s.section)}
                    onPressFavorite={togglePinnedSection}
                    onNavigationPress={onNavigationPress}
                  />
                )}
              >
                {mainSections}
              </Drawer>
            </Drawer>
          </Drawer>
        </View>

        <ManaBar />
      </View>
    ),
    [
      theme,
      JSON.stringify(layout),
      height,
      rightMenuShown,
      mainSections,
      JSON.stringify(pinnedSectionObjects),
      backCallback,
    ],
  );

  const contents = useMemo(
    () => (
      <View
        style={[
          styles.biggerContainer,
          { backgroundColor: theme.colors.background },
        ]}
        onAccessibilityEscape={
          menuShown || rightMenuShown != "none" ? backCallback : undefined
        }
      >
        <Drawer
          open={menuShown}
          onOpen={onMenuOpen}
          onClose={onMenuClose}
          swipeEdgeWidth={
            showBottomMenu
              ? SWIPE_EDGE_WIDTH_LEFT_WITH_BOTTOM_MENU
              : SWIPE_EDGE_WIDTH_DEFAULT
          }
          renderDrawerContent={() => (
            <GameStateFreezer isVisible={menuShown}>
              <MenuContents
                visible={menuShown}
                wide={false}
                pinnedSections={pinnedSectionObjects.map((s) => s.section)}
                onPressFavorite={togglePinnedSection}
                onNavigationPress={onNavigationPress}
              />
            </GameStateFreezer>
          )}
        >
          <View style={[Styles.flex, styles.mainContainerPhone]}>
            <Drawer
              drawerPosition="right"
              open={rightMenuShown == "event"}
              onOpen={onEventMenuOpen}
              onClose={onRightMenuClose}
              swipeEdgeWidth={SWIPE_EDGE_WIDTH_DEFAULT}
              swipeEnabled={rightMenuShown == "event"}
              renderDrawerContent={() => (
                <GameStateFreezer isVisible={rightMenuShown === "event"}>
                  <EventList />
                </GameStateFreezer>
              )}
            >
              <Drawer
                drawerPosition="right"
                open={rightMenuShown == "spell"}
                onOpen={onSpellMenuOpen}
                onClose={onRightMenuClose}
                swipeEdgeWidth={SWIPE_EDGE_WIDTH_DEFAULT}
                renderDrawerContent={() => (
                  <GameStateFreezer isVisible={rightMenuShown === "spell"}>
                    <SpellMenu favoritesOnBottom={putCastSpellOnBottomMenu} />
                  </GameStateFreezer>
                )}
              >
                <View style={Styles.flex}>{mainSections}</View>
              </Drawer>
            </Drawer>
            {showBottomResourceBar && (
              <Animated.View
                style={[
                  {
                    borderTopWidth: 2.0,
                    borderTopColor: theme.colors.outlineVariant,
                  },
                ]}
              >
                <View
                  style={styles.accessibilityHidden}
                  accessible={true}
                  accessibilityLabel="Resources"
                  accessibilityRole="header"
                  accessibilityElementsHidden={false}
                />
                <BottomResourceViewer />
              </Animated.View>
            )}
            {showBottomMenu && (
              <>
                {leftHandedMode && putCastSpellOnBottomMenu && (
                  <View
                    style={{
                      left: -142,
                      bottom: -122,
                      position: "absolute",
                      zIndex: 120,
                    }}
                  >
                    <IconButton
                      accessibilityLabel={t("Cast Spell")}
                      hitSlop={{ top: 50, left: 50, bottom: 20, right: 20 }}
                      onPressIn={onSpellMenuPress}
                      onPress={() => {}}
                      mode="contained"
                      containerColor={theme.colors.primary}
                      style={{
                        borderRadius: 0,
                        borderTopRightRadius: 100,
                        width: 240,
                        height: 168,
                      }}
                      icon={() => (
                        <SimpleLineIcons
                          style={{
                            position: "relative",
                            left: 50,
                            bottom: 52,
                            width: 24,
                            height: 24,
                            overflow: "visible",
                          }}
                          name="magic-wand"
                          color={theme.colors.primaryContainer}
                          size={24}
                        />
                      )}
                    ></IconButton>
                  </View>
                )}
                <View
                  style={{
                    borderTopWidth: 2.0,
                    borderTopColor: theme.colors.outlineVariant,
                  }}
                >
                  <BottomMenu
                    onNavigationPress={onNavigationPress}
                    onSpellMenuPress={onSpellMenuPress}
                    selected={activeSections[0]}
                    leftHanded={leftHandedMode}
                    showCastSpellButton={putCastSpellOnBottomMenu}
                  />
                </View>
                {!leftHandedMode && putCastSpellOnBottomMenu && (
                  <View
                    style={{
                      right: -142,
                      bottom: -122,
                      position: "absolute",
                      zIndex: 120,
                    }}
                  >
                    <IconButton
                      accessibilityLabel={t("Cast Spell")}
                      hitSlop={{ top: 50, left: 50, bottom: 20, right: 20 }}
                      onPressIn={onSpellMenuPress}
                      onPress={() => {}}
                      mode="contained"
                      containerColor={theme.colors.primary}
                      style={{
                        borderRadius: 0,
                        borderTopLeftRadius: 100,
                        width: 240,
                        height: 168,
                      }}
                      icon={() => (
                        <SimpleLineIcons
                          style={{
                            position: "relative",
                            right: 50,
                            bottom: 52,
                            width: 24,
                            height: 24,
                            overflow: "visible",
                          }}
                          name="magic-wand"
                          color={theme.colors.primaryContainer}
                          size={24}
                        />
                      )}
                    ></IconButton>
                  </View>
                )}
              </>
            )}
          </View>
        </Drawer>
        <ManaBar />
      </View>
    ),
    [
      theme,
      menuShown,
      mainSections,
      onMenuPress,
      onNavigationPress,
      onSpellMenuPress,
      height,
      showBottomMenu,
      putCastSpellOnBottomMenu,
      leftHandedMode,
      backCallback,
      JSON.stringify(pinnedSectionObjects),
      showBottomResourceBar,
    ],
  );

  const headerContainerStyle = useMemo(
    () => ({
      borderBottomColor: theme.colors.outlineVariant,
      borderBottomWidth: 2,
      height: auxOptions.compactLayout ? 40 : 48,
    }),
    [auxOptions.compactLayout, theme],
  );

  const menuButton = useMemo(
    () => (
      <TouchableOpacity
        accessibilityLabel="Menu"
        accessibilityRole="button"
        onPressIn={onMenuPress}
        hitSlop={{ top: 20, left: 20, bottom: 20, right: 20 }}
      >
        <MaterialIcons name="menu" size={24} color="#fff" />
      </TouchableOpacity>
    ),
    [onMenuPress],
  );

  const accessibilityContext = useContext(AccessibilityContext);

  const onMagicTap = useCallback(() => {
    if (!activeSections.includes("Exploration")) {
      return;
    }
    const isPaused = hasFlag(stateRef.current, "combat_paused_magic_tap");
    stateRef.current.apply((state) =>
      isPaused
        ? clearFlag("combat_paused_magic_tap")(state)
        : setFlag("combat_paused_magic_tap")(state),
    );
    accessibilityContext.announceForAccessibility(
      isPaused ? t("Resuming Exploration") : t("Pausing Exploration"),
    );
  }, [accessibilityContext, activeSections.join()]);

  return useMemo(() => {
    if (layout.wideLayout) {
      return (
        <GestureHandlerRootView
          style={[
            styles.maxContainer,
            { backgroundColor: theme.colors.background },
          ]}
          onMagicTap={onMagicTap}
        >
          <GameTickProcessor
            setPlayerState={props.setPlayerState}
            onLoadingProgress={props.onLoadingProgress}
          />
          <Appbar.Header style={headerContainerStyle}>
            {centerComponent}
            {!layout.showSpellMenu && (
              <Appbar.Action
                key="spellMenu"
                icon={() => (
                  <SimpleLineIcons
                    name="magic-wand"
                    size={24}
                    color={theme.colors.primary}
                  />
                )}
                onPress={onSpellMenuPress}
              />
            )}
            {!layout.showEventsMenu && (
              <Appbar.Action
                key="eventsMenu"
                icon={() => (
                  <>
                    <MaterialIcons
                      name="notifications"
                      size={24}
                      color={theme.colors.primary}
                    />
                    {ongoingEventCount > 0 && (
                      <Badge style={styles.notificationsBadge}>
                        {ongoingEventCount}
                      </Badge>
                    )}
                  </>
                )}
                onPress={onEventMenuPress}
              />
            )}
          </Appbar.Header>
          {contentsWide}
          <EventOverlay />
        </GestureHandlerRootView>
      );
    }
    return (
      <GestureHandlerRootView
        style={[
          styles.maxContainer,
          { backgroundColor: theme.colors.background },
        ]}
        onMagicTap={onMagicTap}
      >
        <GameTickProcessor
          setPlayerState={props.setPlayerState}
          onLoadingProgress={props.onLoadingProgress}
        />
        <Appbar.Header style={headerContainerStyle}>
          {!layout.showMenu && (
            <Appbar.Action
              key="menu"
              icon={() => (
                <MaterialIcons
                  name="menu"
                  size={24}
                  color={theme.colors.primary}
                />
              )}
              onPress={onMenuPress}
            />
          )}
          {centerComponent}
          {!layout.showSpellMenu &&
            !(showBottomMenu && putCastSpellOnBottomMenu) && (
              <Appbar.Action
                key="spellMenu"
                icon={() => (
                  <SimpleLineIcons
                    name="magic-wand"
                    size={24}
                    color={theme.colors.primary}
                  />
                )}
                onPress={onSpellMenuPress}
              />
            )}
          {!layout.showEventsMenu && (
            <Appbar.Action
              key="eventsMenu"
              icon={() => (
                <>
                  <MaterialIcons
                    name="notifications"
                    size={24}
                    color={theme.colors.primary}
                  />
                  {ongoingEventCount > 0 && (
                    <Badge style={styles.notificationsBadge}>
                      {ongoingEventCount}
                    </Badge>
                  )}
                </>
              )}
              onPress={onEventMenuPress}
            />
          )}
        </Appbar.Header>
        {contents}
        <EventOverlay />
      </GestureHandlerRootView>
    );
  }, [
    props.setPlayerState,
    props.onLoadingProgress,
    onMenuPress,
    onSpellMenuPress,
    onEventMenuPress,
    mainSections,
    ongoingEventCount,
    menuShown,
    rightMenuShown,
    onNavigationPress,
    JSON.stringify(layout),
    height,
    theme,
    contents,
    contentsWide,
    headerContainerStyle,
    menuButton,
    onMagicTap,
    showBottomMenu,
    putCastSpellOnBottomMenu,
  ]);
}

function OfflineProgress(props: { currentTicks: number; totalTicks: number }) {
  const theme = useTheme();
  return (
    <View
      style={[
        styles.offlineProgressContainer,
        { backgroundColor: theme.colors.background },
      ]}
    >
      <ActivityIndicator size="large" />
      <Text>
        {t("Warping through time... {{current}} / {{total}}", {
          current: formatNumber(props.currentTicks, { showDecimals: false }),
          total: formatNumber(props.totalTicks, { showDecimals: false }),
        })}
      </Text>
    </View>
  );
}

function AppContentsInner() {
  const [playerState, setPlayerState] = useState(() => {
    const state = clone(INITIAL_STATE);
    state.temp = createTempState();
    return state;
  });
  const [localUiState, setLocalUiState] = useState<any>({});

  const [processingBigTime, setProcessingBigTime] = useState(false);
  const [totalTicks, setTotalTicks] = useState(0);
  const [currentTicks, setCurrentTicks] = useState(0);

  useEffect(() => {
    if (Platform.OS == "android") {
      // enables edge-to-edge mode
      NavigationBar.setPositionAsync("absolute");
      // transparent backgrounds to see through
      NavigationBar.setBackgroundColorAsync("#ffffff00");
      // do not resize safe area
      NavigationBar.setBehaviorAsync("overlay-swipe");
    }
  }, []);

  const onLoadingProgress = useCallback(
    (currentTicks: number, totalTicks: number) => {
      setCurrentTicks(currentTicks);
      setTotalTicks(totalTicks);
      if (currentTicks == totalTicks) {
        setProcessingBigTime(false);
      } else {
        setProcessingBigTime(true);
      }
    },
    [],
  );

  const uiContextValue = useMemo(() => {
    return {
      getUIState: (id: string) => {
        return localUiState?.[id];
      },
      setUIState: (id: string, value: any) => {
        localUiState[id] = value;
        setLocalUiState(localUiState);
      },
    };
  }, [localUiState, setLocalUiState]);

  type TooltipData = {
    content: TooltipContent;
    position: TooltipPosition;
    measurementId: number;
    postMeasurementId: number;
    mouseEnterTimestamp: number;
    mouseLeaveTimestamp: number;
  };
  const [tooltips, setTooltips] = useState<Record<string, TooltipData>>({});

  const tooltipsRef = useRef(tooltips);
  tooltipsRef.current = tooltips;
  const setTooltip = useCallback(
    (
      id: string,
      tooltip: TooltipContent,
      position: TooltipPosition,
      measurementId?: number,
    ) => {
      const newTooltips = Object.assign({}, tooltipsRef.current);
      if (!newTooltips[id]) {
        newTooltips[id] = {
          content: tooltip,
          position: position,
          postMeasurementId: 1,
          measurementId: 0,
          mouseEnterTimestamp: 0,
          mouseLeaveTimestamp: 0,
        };
      }
      newTooltips[id].content = tooltip;
      newTooltips[id].position = position;
      if (measurementId != null) {
        newTooltips[id].postMeasurementId = measurementId;
      }
      tooltipsRef.current = newTooltips;
      setTooltips(newTooltips);
    },
    [setTooltips],
  );
  const clearTooltip = useCallback(
    (id: string) => {
      const newTooltips = Object.assign({}, tooltipsRef.current);
      const found = newTooltips[id] != null;
      if (!found) {
        return;
      }
      newTooltips[id].content = null;
      tooltipsRef.current = newTooltips;
      setTooltips(newTooltips);
    },
    [setTooltips],
  );
  const onUnmountTooltip = useCallback(
    (id: string) => {
      const newTooltips = tooltipsRef.current;
      const found = newTooltips[id] != null;
      if (!found) {
        return;
      }
      delete newTooltips[id];
      delete tooltipsRef.current[id];
      setTooltips(Object.assign({}, newTooltips));
    },
    [setTooltips],
  );
  const onMouseLeave = useCallback(
    (id: string) => {
      if (!tooltipsRef.current?.[id]) {
        return;
      }
      const newTooltips = Object.assign({}, tooltipsRef.current);
      newTooltips[id].mouseLeaveTimestamp = Date.now();
      tooltipsRef.current = newTooltips;
      setTooltips(newTooltips);
    },
    [setTooltips],
  );
  const onMouseEnter = useCallback(
    (id: string, measurementId: number) => {
      const newTooltips = Object.assign({}, tooltipsRef.current);
      if (!newTooltips[id]) {
        newTooltips[id] = {
          content: null,
          position: { containerWidth: 1 },
          postMeasurementId: 1,
          measurementId: 0,
          mouseEnterTimestamp: 0,
          mouseLeaveTimestamp: 0,
        };
      }
      newTooltips[id].measurementId = measurementId;
      newTooltips[id].mouseEnterTimestamp = Date.now();
      tooltipsRef.current = newTooltips;
      setTooltips(newTooltips);
    },
    [setTooltips],
  );
  let tooltip: null | TooltipData = null;
  for (let tooltipId in tooltips) {
    if (
      tooltips[tooltipId].mouseEnterTimestamp >
        tooltips[tooltipId].mouseLeaveTimestamp &&
      tooltips[tooltipId].measurementId ===
        tooltips[tooltipId].postMeasurementId &&
      tooltips[tooltipId].content != null
    ) {
      tooltip = tooltips[tooltipId];
      break;
    }
  }
  const tooltipContextValue = useMemo(() => {
    return {
      tooltip: tooltip?.content,
      position: tooltip?.position ?? { containerWidth: 1 },
    };
  }, [tooltip?.content, tooltip?.position]);
  const tooltipControlContextValue = useMemo(() => {
    return {
      setTooltip: setTooltip,
      clearTooltip: clearTooltip,
      onMouseLeave: onMouseLeave,
      onMouseEnter: onMouseEnter,
      onUnmountTooltip: onUnmountTooltip,
    };
  }, [setTooltip, clearTooltip, onMouseLeave, onMouseEnter, onUnmountTooltip]);

  const [loading, setLoading] = useState(true);
  const [criticalError, setCriticalError] = useState(false);
  const [lessCriticalError, setLessCriticalError] = useState(false);

  const playerStateRef = useRef(playerState);
  playerStateRef.current = playerState;

  const apply = useCallback(
    (transform: GameStateTransform) => {
      const newState = applyAllTransforms(playerStateRef.current, transform);
      newState.global.appVersionTimestamp = getCurrentVersionTimestamp() || 0;
      newState.global.appVersionString = getCurrentVersion() ?? "0.0.0";
      newState.global.isFullVersionSave = true;

      setPlayerState(newState);
    },
    [playerStateRef, setPlayerState],
  );

  const overwrite = useCallback(
    (transform: GameStateTransform) => {
      let transformedState = transform(playerStateRef.current);
      transformedState = clearAllCaches(transformedState);
      setPlayerState(transformedState);
    },
    [playerStateRef, setPlayerState],
  );

  const stateContextValue: StateContextValue = useMemo(() => {
    return {
      ...playerState,
      apply: apply,
      overwrite: overwrite,
    };
  }, [playerState, apply, overwrite]);

  useEffect(() => {
    updateGameState(stateContextValue);
  }, [stateContextValue]);

  useEffect(() => {
    const fetchData = async () => {
      try {
        let loadedState = await loadData();
        loadedState = clearAllCaches(loadedState);
        setPlayerState(loadedState);
        await nextFrame();
        setLoading(false);
      } catch (error) {
        console.error("Error loading data");
        setCriticalError(true);
      }
    };

    fetchData();
  }, []);

  const [lastTimeFailedToSave, setLastTimeFailedToSave] = useState(0);

  useEffect(() => {
    const storeData = async () => {
      if (lessCriticalError || criticalError) {
        // Do not save if there's an error!!
        return;
      }
      try {
        await saveData(playerState);
        if (lastTimeFailedToSave > 0) {
          setLastTimeFailedToSave(0);
        }
      } catch (error) {
        console.error("Error saving data", error);
        if (
          lastTimeFailedToSave > 0 &&
          lastTimeFailedToSave <
            Math.floor(getSecondsPlayed(playerState) / 5.0) - 10.0
        ) {
          setLessCriticalError(true);
        } else {
          if (lastTimeFailedToSave <= 0) {
            setLastTimeFailedToSave(
              Math.floor(getSecondsPlayed(playerState) / 5.0),
            );
          }
        }
      }
    };
    if (!loading && !processingBigTime) {
      storeData();
    }
  }, [
    Math.floor(getSecondsPlayed(playerState) / 5.0),
    loading,
    processingBigTime,
    lastTimeFailedToSave,
    setLastTimeFailedToSave,
    lessCriticalError,
    criticalError,
  ]);

  const [isTransition, setTransition] = useTransition();

  const theme = getThemeByElementAndDarkMode(
    stateContextValue.options.overrideElementDisplay != "no"
      ? stateContextValue.options.overrideElementDisplay
      : stateContextValue.run.primaryElement,
    stateContextValue.options.themeMode === "dark",
  );

  const auxOptions = useMemo(
    () => ({
      compactLayout: stateContextValue.options.compactUI,
    }),
    [stateContextValue.options.compactUI],
  );

  const shouldShowIntro = stateContextValue.run.primaryElement == undefined;

  const [showIntro, setShowIntro] = useState(shouldShowIntro);

  useEffect(() => {
    if (showIntro != shouldShowIntro) {
      setTimeout(() => setShowIntro(shouldShowIntro), 0);
    }
  }, [showIntro, shouldShowIntro, setTransition, setShowIntro]);

  const { height, width } = useWindowDimensions();

  const copyToClipboard = useCallback(async () => {
    const serializedData = await serializeSaveData(playerStateRef.current);
    await Clipboard.setStringAsync(serializedData);
  }, []);

  const saveToFileCallback = useCallback(async () => {
    const result = await saveToFile(playerStateRef.current);
  }, []);

  const copyAsyncStorageToClipboard = useCallback(async () => {
    const jsonValue = await AsyncStorage.getItem("GameState");
    await Clipboard.setStringAsync(jsonValue ?? "null");
  }, []);

  const criticalErrorView = useMemo(
    () => (
      <View style={styles.errorContainer}>
        <View style={styles.errorWrapper}>
          <View style={styles.errorHeader}>
            <Text variant="headlineMedium">
              {t("Something went very wrong!")}
            </Text>
          </View>
          <Text>
            {t(`It looks like an unexpected error happened while loading the game.
Please try closing the app and opening it again. If the error
persists, please contact magic.research.game@gmail.com and send your
debug saved data for further support.`)}
          </Text>
          <PaperButton
            onPress={copyAsyncStorageToClipboard}
            style={styles.saveButton}
          >
            {t("Copy debug save data to clipboard")}
          </PaperButton>
        </View>
      </View>
    ),
    [copyAsyncStorageToClipboard],
  );

  const errorView = useMemo(
    () => (
      <View style={styles.errorContainer}>
        <View style={styles.errorWrapper}>
          <View style={styles.errorHeader}>
            <Text variant="headlineMedium">{t("Something went wrong!")}</Text>
          </View>
          <Text>
            {t(`It looks like an unexpected error happened. Please try closing the
app and opening it again. If the error persists, please contact
magic.research.game@gmail.com and send your saved data for further
support.`)}
          </Text>
          <PaperButton onPress={copyToClipboard} style={styles.saveButton}>
            {t("Copy savefile to clipboard")}
          </PaperButton>
          {SAVE_TO_FILE_SUPPORTED && (
            <PaperButton onPress={saveToFileCallback} style={styles.saveButton}>
              {t("Save to file")}
            </PaperButton>
          )}
        </View>
      </View>
    ),
    [copyToClipboard, saveToFileCallback],
  );

  const [accessibilityItems, setAccessibilityItems] = useState<
    { text: string; key: string }[]
  >([]);

  const wakeLock = useMemo(
    () => (stateContextValue.options.wakeLock ? <KeepAwake /> : null),
    [stateContextValue.options.wakeLock],
  );

  const accessibilityItemsRef = useRef(accessibilityItems);
  accessibilityItemsRef.current = accessibilityItems;

  const accessibilityContext = useMemo(() => {
    return Platform.select({
      default: {
        announceForAccessibility: (announcement: string) =>
          AccessibilityInfo.announceForAccessibilityWithOptions(announcement, {
            queue: false,
          }),
      },
      web: {
        announceForAccessibility: (announcement: string) => {
          const newItem = { text: announcement, key: Math.random().toString() };
          const oldItem = accessibilityItemsRef.current?.[0];
          if (oldItem?.text == newItem.text) {
            setAccessibilityItems([]);
            // Fix for same attack used in quick succession...
            setTimeout(() => setAccessibilityItems([newItem]), 100);
          } else {
            setAccessibilityItems([newItem]);
          }
        },
      },
    });
  }, []);

  const [visibilityState, setVisibilityState] = useState("visible");

  useEffect(() => {
    if (Platform.OS != "web") {
      return;
    }

    const onVisibilityChange = () => {
      setVisibilityState(document.visibilityState);
    };

    document.addEventListener("visibilitychange", onVisibilityChange);

    return () =>
      document.removeEventListener("visibilitychange", onVisibilityChange);
  }, []);

  useEffect(() => {
    if (
      Platform.OS == "web" &&
      visibilityState == "visible" &&
      JSON.stringify(getLayoutType(stateContextValue, height, width)) ==
        JSON.stringify(getLayoutType(stateContextValue, height, width + 1))
    ) {
      // Hack fix for alt tab out and back into window breaking screen readers
      // Does not work if window is maximized
      window.resizeBy(1, 0);
      window.resizeBy(-1, 0);
    }
  }, [visibilityState]);

  const offlineProgressView = useMemo(
    () => (
      <View style={[{ flex: 1 }, !processingBigTime && { display: "none" }]}>
        <SafeAreaView style={[styles.safeAreaContainer, styles.centerAligned]}>
          <OfflineProgress
            currentTicks={currentTicks}
            totalTicks={totalTicks}
          />
        </SafeAreaView>
      </View>
    ),
    [currentTicks, totalTicks, processingBigTime],
  );

  const accessibilityView = useMemo(
    () =>
      Platform.OS === "web" &&
      visibilityState == "visible" && (
        <div style={styles.accessibilityView} aria-live="polite" role="status">
          {accessibilityItems.map((item) => (
            <div key={item.key} role="none">
              <UtilityText key={item.key}>{item.text}</UtilityText>
            </div>
          ))}
        </div>
      ),
    [accessibilityItems.map((item) => item.key).join(), visibilityState],
  );

  const mainView = useMemo(
    () => (
      <View
        style={[{ flex: 1 }, processingBigTime && { display: "none" }]}
        nativeID={theme.dark ? "dark" : "light"}
      >
        <GameStateFreezer isVisible={!showIntro && !processingBigTime}>
          <MainAppContents
            setPlayerState={setPlayerState}
            onLoadingProgress={onLoadingProgress}
          />
        </GameStateFreezer>
      </View>
    ),
    [
      processingBigTime,
      theme.dark,
      showIntro,
      setPlayerState,
      onLoadingProgress,
    ],
  );

  const internalToPaperContextComponent = useMemo(
    () => (
      <PaperProvider theme={theme}>
        <ErrorBoundary errorFallback={errorView}>
          <AccessibilityContext.Provider value={accessibilityContext}>
            <TooltipControlContext.Provider value={tooltipControlContextValue}>
              <AuxOptionsContext.Provider value={auxOptions}>
                <TooltipContext.Provider value={EMPTY_TOOLTIP_CONTEXT}>
                  <ZoomChecker />
                  <StatusBar style={theme.dark ? "light" : "dark"} />
                  <FullScreenListener />
                  <SaveDataBackupper />
                  <OrientationLockManager />
                  <DebugUPSOverlay />
                  {wakeLock}
                  {accessibilityView}
                  {offlineProgressView}
                  {mainView}
                </TooltipContext.Provider>
                {!stateContextValue.options.hideTooltips && <TooltipDisplay />}
              </AuxOptionsContext.Provider>
            </TooltipControlContext.Provider>
          </AccessibilityContext.Provider>
        </ErrorBoundary>
      </PaperProvider>
    ),
    [
      theme,
      errorView,
      accessibilityContext,
      tooltipControlContextValue,
      wakeLock,
      accessibilityView,
      offlineProgressView,
      mainView,
      auxOptions,
      stateContextValue?.options?.hideTooltips,
    ],
  );

  const internalToUIContextComponent = useMemo(
    () => (
      <UIContext.Provider value={uiContextValue}>
        <TooltipContext.Provider value={tooltipContextValue}>
          {internalToPaperContextComponent}
        </TooltipContext.Provider>
      </UIContext.Provider>
    ),
    [
      initialWindowMetrics,
      tooltipContextValue,
      uiContextValue,
      internalToPaperContextComponent,
    ],
  );

  if (criticalError) {
    return criticalErrorView;
  }

  if (lessCriticalError) {
    return errorView;
  }

  if (loading || shouldShowIntro != showIntro) {
    return (
      <PaperProvider theme={theme}>
        <ErrorBoundary errorFallback={errorView}>
          <AccessibilityContext.Provider value={accessibilityContext}>
            <Loading />
          </AccessibilityContext.Provider>
        </ErrorBoundary>
      </PaperProvider>
    );
  }

  if (showIntro) {
    return (
      <TooltipContext.Provider value={tooltipContextValue}>
        <PaperProvider theme={theme}>
          <ErrorBoundary errorFallback={errorView}>
            <AccessibilityContext.Provider value={accessibilityContext}>
              <TooltipControlContext.Provider
                value={tooltipControlContextValue}
              >
                <AuxOptionsContext.Provider value={auxOptions}>
                  {/* <StateContext.Provider value={stateContextValue}> */}
                  <UIContext.Provider value={uiContextValue}>
                    <ZoomChecker />
                    <StatusBar style={theme.dark ? "light" : "dark"} />
                    <FullScreenListener />
                    {wakeLock}
                    <IntroContents />
                    {!stateContextValue.options.hideTooltips && (
                      <TooltipDisplay />
                    )}
                  </UIContext.Provider>
                  {/* </StateContext.Provider> */}
                </AuxOptionsContext.Provider>
              </TooltipControlContext.Provider>
            </AccessibilityContext.Provider>
          </ErrorBoundary>
        </PaperProvider>
      </TooltipContext.Provider>
    );
  }

  return (
    // <StateContext.Provider value={stateContextValue}>
    internalToUIContextComponent
    // </StateContext.Provider>
  );
}

export default function AppContents() {
  const { height } = useWindowDimensions();
  const safeAreaProviderStyle = useMemo(
    () => Platform.OS === "web" && { height: height, flex: "auto" },
    [height],
  );
  return (
    <SafeAreaProvider
      initialMetrics={initialWindowMetrics}
      // @ts-expect-error In react-native-web, flex: "auto" works
      style={safeAreaProviderStyle}
    >
      <AppContentsInner />
    </SafeAreaProvider>
  );
}

