import { loadDoubleElementSynchroBonuses } from "./bonuses/DoubleElementSynchroBonuses";
import { loadAirPoisonSynchroBonus } from "./bonuses/air/AirPoisonSynchroBonus";
import { loadEarthAirSynchroBonus } from "./bonuses/earth/EarthAirSynchroBonus";
import { loadEarthPoisonSynchroBonus } from "./bonuses/earth/EarthPoisonSynchroBonus";
import { loadEarthWaterSynchroBonus } from "./bonuses/earth/EarthWaterSynchroBonus";
import { loadFireAirSynchroBonus } from "./bonuses/fire/FireAirSynchroBonus";
import { loadFireEarthSynchroBonus } from "./bonuses/fire/FireEarthSynchroBonus";
import { loadFirePoisonSynchroBonus } from "./bonuses/fire/FirePoisonSynchroBonus";
import { loadFireWaterSynchroBonus } from "./bonuses/fire/FireWaterSynchroBonus";
import { loadWaterAirSynchroBonus } from "./bonuses/water/WaterAirSynchroBonus";
import { loadWaterPoisonSynchroBonus } from "./bonuses/water/WaterPoisonSynchroBonus";

export function loadSynchroDemo() {
  loadDoubleElementSynchroBonuses();

  // Fire
  loadFireEarthSynchroBonus();
  loadFireWaterSynchroBonus();
  loadFireAirSynchroBonus();
  loadFirePoisonSynchroBonus();

  // Earth
  loadEarthWaterSynchroBonus();
  loadEarthAirSynchroBonus();
  loadEarthPoisonSynchroBonus();

  // Water
  loadWaterAirSynchroBonus();
  loadWaterPoisonSynchroBonus();

  // Air
  loadAirPoisonSynchroBonus();
}
