import { formatMultiplier } from "../../../../../utils/FormattingUtils";
import { t } from "../../../../../utils/I18n";
import { ActionEffect } from "../../../../action/Action";
import { GameState } from "../../../../GameState";
import { Resource } from "../../../../Resources";
import { SpellElement } from "../../../../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../../../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../../../transformation/TransformationTags";
import { EquipmentSlot, getEquippedItem } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class WandOfPoison extends EquippableItem {
  getId(): string {
    return "wandOfPoison";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Hand;
  }
  getPicture() {
    return require("./wandOfPoison.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Wand of Poison");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A magical wand fine tuned for Poison. Increases power of Poison spells.",
    );
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 5000;
  }

  getAttackBonusBase(params: ItemParams): number {
    return 60;
  }

  getManaRegenBonusBase(params: ItemParams): number {
    return 60;
  }

  getMaxManaBonusBase(params: ItemParams): number {
    return 1200;
  }

  protected getBaseExtraItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 0.15,
        valueType: TransformationValueType.Percent,
      },
    };
  }

  getEffectExtra(state: GameState, params: ItemParams): string {
    const effects = this.getItemEffects(state, params);
    const explanations = this.getItemEffectExplanations(state, params);
    return t(
      "^{{magnitude}}^<{{explanation}}> power of Poison elemental spells",
      {
        magnitude: formatMultiplier(1.0 + effects.magnitude),
        explanation: explanations.magnitude,
      },
    );
  }
}

export const wandOfPoison = new WandOfPoison();

registerTransformation(
  [
    [
      TransformationTags.ActionEffect,
      TransformationTags.ActionMagnitude,
      SpellElement.Poison,
    ],
    [
      TransformationTags.ActionEffect,
      TransformationTags.TemporaryEffectMagnitude,
      SpellElement.Poison,
    ],
  ],
  wandOfPoison.getId(),
  wandOfPoison.getName(wandOfPoison.getDefaultParams()),
  TransformationType.Multiplier,
  (state) =>
    getEquippedItem(state, EquipmentSlot.Hand)?.itemId == wandOfPoison.getId()
      ? wandOfPoison.getEquippedItemEffect(state, "magnitude").magnitude + 1.0
      : 1.0,
);

class TransmuteWandOfPoison extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return wandOfPoison;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Poison: 25,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 600,
        PoisonEssence: 50000,
      },
      items: {
        elementalPrismPoison: 6,
        copperIngot: 20,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Poison;
  }
}

export const transmuteWandOfPoison = new TransmuteWandOfPoison();
