import { t } from "../../utils/I18n";
import { modifyTargetCurrentHP } from "../exploration/Exploration";
import {
  CombatActionResultListeners,
  pushCombatActionResult,
} from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Cursed extends TemporaryEffect {
  getId(): string {
    return "cursed";
  }
  getDisplayName(): string {
    return t("Cursed");
  }
  getIcon() {
    return require("./cursed.png");
  }
  isBeneficial(): boolean {
    return false;
  }
  isCombatOnly(): boolean {
    return true;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "Explicit healing effects, in addition to the healing, cause damage of the same amount",
    );
  }
}

export const cursed = new Cursed();

export function loadCursed() {
  TemporaryEffects.register(cursed);

  CombatActionResultListeners.register((state, result) => {
    if (
      result.damage < 0 &&
      hasTemporaryEffect(state, cursed.getId(), result.target)
    ) {
      state = modifyTargetCurrentHP(
        state,
        result.target,
        result.damage,
        cursed.getDisplayName(),
      );
      const actionResult = {
        id: Math.random().toString(),
        damage: -result.damage,
        isCrit: false,
        isHit: true,
        time: getSecondsPlayed(state),
        source: result.target,
        target: result.target,
        tags: [cursed.getId()],
      };
      state = pushCombatActionResult(state, actionResult);
    }
    return state;
  });
}
