import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";

class GlitteringStalls extends Enhancement {
  getId(): string {
    return "glitteringStalls";
  }
  getBaseName(): string {
    return t("Glittering Stalls");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_OTHER;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 300,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 2,
    };
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Enhance your Market Stalls so that the goods sold appear to be of higher quality than usual.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("+100%:coins: obtained from items sold from Market Stalls");
  }
}

export const glitteringStalls = new GlitteringStalls();

registerTransformation(
  [[TransformationTags.SaleStallRevenue]],
  glitteringStalls.getId(),
  glitteringStalls.getBaseName(),
  TransformationType.Multiplier,
  (state) => (glitteringStalls.isPurchased(state) ? 2.0 : 1.0),
);
