import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import SlimeDrop from "../../../items/material/SlimeDrop";
import Wildgrass from "../../../items/material/Wildgrass";
import { BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class Slime extends Enemy {
  getId(): string {
    return "slime";
  }

  getName(): string {
    return t("Slime");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 50,
      attack: 15,
      defense: 5,
      attackDelay: 3.2,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./slime.png");
  }

  getLevel(): number {
    return 1;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 12;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 3;
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 1,
        chance: 0.1,
      },
      {
        itemId: SlimeDrop.getId(),
        amount: 1,
        chance: 0.5,
      },
    ];
  }
}

export default new Slime();
