import memoize from "sonic-memoize";
import { valuesOf } from "../../utils/CoreUtils";
import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { ActionEffect } from "../action/Action";
import { getTotalAmountOfItem } from "../items/Inventory";
import { Item } from "../items/Item";
import { Items } from "../items/Items";
import { SpellElement } from "../spells/Elements";
import { Spell } from "../spells/Spell";
import {
  applyTransformationsCached,
  explainTransformationsText,
} from "../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../transformation/TransformationTags";

function generateEffect(value: number) {
  if (value > 0) {
    return {
      value,
      valueType: TransformationValueType.Percent,
    };
  } else {
    return {
      value: 1.0 / (1.0 + value) - 1.0,
      valueType: TransformationValueType.Divisor,
    };
  }
}

export function generateSpellcraftMultiplier(
  baseValue: number,
  transformedValue: number,
) {
  if (transformedValue == null) {
    return 1.0;
  }

  if (baseValue > 0) {
    return 1.0 + transformedValue;
  } else {
    return 1.0 / (transformedValue + 1.0);
  }
}

export abstract class Spellcraft {
  getId(): string {
    return "spellcraft-" + this.getBaseItem().getId();
  }

  abstract getBaseItem(): Item;

  getName(): string {
    return this.getBaseItem().getDefaultName();
  }

  getPicture() {
    return this.getBaseItem().getPicture();
  }

  getTags(): string[] {
    return [TransformationTags.Spellcraft];
  }

  tagsByEffect: Record<string, string[]> = {};
  getTagsByEffect(key: string, baseEffect: ActionEffect): string[] {
    if (this.tagsByEffect?.[key] == null) {
      this.tagsByEffect[key] = [
        ...this.getTags(),
        this.getId(),
        ...(baseEffect.tags ?? []),
        TransformationTags.SpellcraftEffect,
      ];
    }
    return this.tagsByEffect[key];
  }

  protected getBaseWizardPowerMultiplier(): number {
    return 0;
  }

  protected getBasePowerMultiplier(): number {
    return 0;
  }

  protected getBaseCooldownMultiplier(): number {
    return 0;
  }

  protected getBaseTemporaryEffectDurationMultiplier(): number {
    return 0;
  }

  protected getBaseManaCostMultiplier(): number {
    return 0;
  }

  getBaseMultipliers(): Record<string, number> {
    return {
      wizardPower: this.getBaseWizardPowerMultiplier(),
      power: this.getBasePowerMultiplier(),
      cooldown: this.getBaseCooldownMultiplier(),
      temporaryEffectDuration: this.getBaseTemporaryEffectDurationMultiplier(),
      manaCost: this.getBaseManaCostMultiplier(),
    };
  }

  requiresSpellsWithParameterizedEffects(): boolean {
    return this.getBasePowerMultiplier() != 0;
  }

  requiresSpellsWithCooldown(): boolean {
    return this.getBaseCooldownMultiplier() != 0;
  }

  requiresSpellsWithTemporaryEffect(): boolean {
    return this.getBaseTemporaryEffectDurationMultiplier() != 0;
  }

  requiresSpellsWithFixedManaCost(): boolean {
    return this.getBaseManaCostMultiplier() != 0;
  }

  isSpellCompatible(spell: Spell): boolean {
    if (this.requiresSpellsWithFixedManaCost() && !spell.hasFixedManaCost()) {
      return false;
    }

    if (
      this.requiresSpellsWithParameterizedEffects() &&
      !valuesOf(spell.getCachedBaseActionEffects()).some((effect) =>
        effect.tags?.includes?.(TransformationTags.ActionMagnitude),
      ) &&
      !valuesOf(spell.getCachedBaseActionEffects()).some((effect) =>
        effect.tags?.includes?.(TransformationTags.TemporaryEffectMagnitude),
      ) &&
      !valuesOf(spell.getCachedBaseActionEffects()).some((effect) =>
        effect.tags?.includes?.(TransformationTags.ChannelingEfficiency),
      )
    ) {
      return false;
    }

    if (this.requiresSpellsWithCooldown() && spell.getBaseCooldown() <= 0) {
      return false;
    }

    if (
      this.requiresSpellsWithTemporaryEffect() &&
      !valuesOf(spell.getCachedBaseActionEffects()).some((effect) =>
        effect.tags?.includes?.(TransformationTags.TemporaryEffectDuration),
      )
    ) {
      return false;
    }

    return true;
  }

  canAfford(state: GameState): boolean {
    return getTotalAmountOfItem(this.getBaseItem(), state) >= 1000;
  }

  getCachedBaseBonusEffects = memoize(() => {
    const effects: Record<string, ActionEffect> = {};
    if (this.getBaseWizardPowerMultiplier() != 0) {
      effects.wizardPower = generateEffect(this.getBaseWizardPowerMultiplier());
    }
    if (this.getBasePowerMultiplier() != 0) {
      effects.power = generateEffect(this.getBasePowerMultiplier());
    }
    if (this.getBaseCooldownMultiplier() != 0) {
      effects.cooldown = generateEffect(this.getBaseCooldownMultiplier());
    }
    if (this.getBaseTemporaryEffectDurationMultiplier() != 0) {
      effects.temporaryEffectDuration = generateEffect(
        this.getBaseTemporaryEffectDurationMultiplier(),
      );
    }
    if (this.getBaseManaCostMultiplier() != 0) {
      effects.manaCost = generateEffect(this.getBaseManaCostMultiplier());
    }

    return effects;
  });

  getEffect(state: GameState): string {
    const effects = this.getBonusEffects(state);
    const effectExplanations = this.getBonusEffectExplanations(state);

    const multipliers: Record<string, number> = {
      wizardPower: generateSpellcraftMultiplier(
        this.getBaseWizardPowerMultiplier(),
        effects.wizardPower,
      ),
      power: generateSpellcraftMultiplier(
        this.getBasePowerMultiplier(),
        effects.power,
      ),
      cooldown: generateSpellcraftMultiplier(
        this.getBaseCooldownMultiplier(),
        effects.cooldown,
      ),
      temporaryEffectDuration: generateSpellcraftMultiplier(
        this.getBaseTemporaryEffectDurationMultiplier(),
        effects.temporaryEffectDuration,
      ),
      manaCost: generateSpellcraftMultiplier(
        this.getBaseManaCostMultiplier(),
        effects.manaCost,
      ),
    };

    const bonusStrings = Object.keys(multipliers).map((stat) => {
      let value = multipliers?.[stat];
      if (value == 1.0) {
        return null;
      }
      const explanation = effectExplanations?.[stat];
      switch (stat) {
        case "wizardPower":
          return t("^{{value}}^<{{explanation}}> :wizardpower: cost", {
            value: formatMultiplier(value),
            explanation,
          });
        case "power":
          return t("^{{value}}^<{{explanation}}> spell power", {
            value: formatMultiplier(value),
            explanation,
          });
        case "cooldown":
          return t("^{{value}}^<{{explanation}}> spell cooldown", {
            value: formatMultiplier(value),
            explanation,
          });
        case "temporaryEffectDuration":
          return t("^{{value}}^<{{explanation}}> duration of spell effects", {
            value: formatMultiplier(value),
            explanation,
          });
        case "manaCost":
          return t("^{{value}}^<{{explanation}}> :mana: cost", {
            value: formatMultiplier(value),
            explanation,
          });
      }
    });

    return bonusStrings
      .filter((bonusString) => bonusString != null)
      .join(t(", "));
  }

  getBonusEffect(state: GameState, effectId: string): number {
    const baseEffects = this.getCachedBaseBonusEffects();
    const baseEffect = baseEffects[effectId];
    if (baseEffect == null) {
      return 0.0;
    }
    return applyTransformationsCached(
      this.getTagsByEffect(effectId, baseEffect),
      state,
      baseEffect.value,
    );
  }

  getBonusEffects(state: GameState, amount?: number): Record<string, number> {
    const baseEffects = this.getCachedBaseBonusEffects();
    const finalEffects: Record<string, number> = {};
    for (let key in baseEffects) {
      finalEffects[key] = this.getBonusEffect(state, key);
    }
    return finalEffects;
  }

  bonusEffectExplanationParams = memoize((baseEffect: ActionEffect) => ({
    valueType: baseEffect.valueType,
    unit: baseEffect.unit,
  }));

  getBonusEffectExplanations(
    state: GameState,
    onlyId?: string,
  ): Record<string, string> {
    const baseEffects = this.getCachedBaseBonusEffects();
    const finalEffects: Record<string, string> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = explainTransformationsText(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.bonusEffectExplanationParams(baseEffect),
      );
    }
    return finalEffects;
  }
}

export abstract class PrismBasedSpellcraft extends Spellcraft {
  protected abstract getElement(): SpellElement;

  getBaseItem(): Item {
    return Items.getById("elementalPrism" + this.getElement());
  }

  protected getBaseWizardPowerMultiplier(): number {
    return 1.0;
  }
}

export abstract class JewelBasedSpellcraft extends Spellcraft {
  protected abstract getElement(): SpellElement;

  getBaseItem(): Item {
    return Items.getById("elementalJewel" + this.getElement());
  }

  protected getBaseWizardPowerMultiplier(): number {
    return 2.0;
  }
}
