import memoize from "sonic-memoize";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource, clearResourceCapCache } from "../../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { SpellElement } from "../../spells/Elements";
import { registerSpell } from "../../spells/Spells";
import {
  TransformationType,
  applyTransformations,
  explainTransformationsText,
  registerTransformation,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import {
  BuildingAmountListeners,
  Buildings,
  getBuildingAmount,
} from "../Buildings";

class Monstruarium extends Building {
  getId(): string {
    return "monstruarium";
  }

  getName(): string {
    return t("Monstruarium");
  }

  perBuildingCapTags = [
    TransformationTags.PerBuildingCap,
    Resource.Monstium,
    this.getId(),
  ];

  getPerStorageBonusCap(state: GameState): number {
    return applyTransformations(this.perBuildingCapTags, state, 3000, {
      resource: Resource.Monstium,
    });
  }

  explanationParams = memoize(() => ({
    resource: Resource.Monstium,
    unit: t(" Max:monstium:"),
  }));

  getPerStorageBonusCapExplanation(state: GameState): string {
    return explainTransformationsText(
      this.perBuildingCapTags,
      state,
      3000,
      this.explanationParams(),
    );
  }

  getDisplayDescription(state: GameState): string {
    return t("A special kind of storage, built specifically for :monstium:.");
  }

  getDisplayEffect(state: GameState): string {
    return t("^+{{amount}}^<{{explanation}}> Max:monstium:", {
      amount: formatNumber(this.getPerStorageBonusCap(state)),
      explanation: this.getPerStorageBonusCapExplanation(state),
    });
  }

  getBaseLandRequired(): number {
    return 3;
  }

  canTurnOff(): boolean {
    return false;
  }
}

export const monstruarium = new Monstruarium();

class BuildMonstruarium extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.STORAGE] };
  }

  getBuilding(): Monstruarium {
    return monstruarium;
  }

  shouldNeverHaveDemolishBlockers(): boolean {
    return false;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return { PoisonEssence: 6000, WaterEssence: 4000, EarthEssence: 4000 };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      PoisonEssence: 1.4,
      WaterEssence: 1.4,
      EarthEssence: 1.4,
    };
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.MonstruariumsUnlocked);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Poison;
  }
}

export const buildMonstruarium = new BuildMonstruarium();

export function loadMonstruarium() {
  Buildings.register(monstruarium);
  registerSpell(buildMonstruarium);

  registerTransformation(
    [[TransformationTags.Cap, Resource.Monstium]],
    "monstruariumMonstium",
    t("Monstruarium"),
    TransformationType.Addition,
    (state, params) => {
      return (
        getBuildingAmount(state, monstruarium) *
        monstruarium.getPerStorageBonusCap(state)
      );
    },
  );

  BuildingAmountListeners.register((state, building) => {
    if (building == monstruarium) {
      state = clearResourceCapCache(state);
    }
    return state;
  }, monstruarium.getId());
}
