import {
  Entypo,
  MaterialCommunityIcons,
  MaterialIcons,
} from "@expo/vector-icons";
import React, { useMemo } from "react";
import { StyleSheet, TouchableWithoutFeedback, View } from "react-native";
import { useTheme } from "react-native-paper";
import { DiviningTheWorld } from "../../backend/events/storyline/desert/DiviningTheWorldStoryline";
import { SpellElement, getElementLevel } from "../../backend/spells/Elements";
import { Storyline } from "../../backend/storylines/Storyline";
import { t } from "../../utils/I18n";
import { Markdown } from "./Markdown";
import { DescriptionViewer } from "./Miscellaneous";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

export function StorylineContainer(props: {
  storyline: Storyline;
  collapsed?: boolean;
  onPressExpand?: () => void;
}) {
  const state = useGameState();
  const storyline = props.storyline;

  const isCompleted = storyline.isCompleted(state);
  const isBonusActive = storyline.isBonusActive(state);
  const isHintRevealed = storyline.isHintRevealed(state);
  const isHintUnlockable =
    storyline.isHintUnlockable(state) && DiviningTheWorld.isBonusActive(state);

  const isEnabled =
    isCompleted || isBonusActive || isHintRevealed || isHintUnlockable;

  const descriptionContainer = useMemo(() => {
    let descriptionContents = t("???");
    if (isHintUnlockable) {
      descriptionContents = t(
        "You may learn more about this Storyline if your Mind is Lv{{level}} or higher...",
        { level: storyline.mindRequirementForHint },
      );
    }
    if (isHintRevealed) {
      descriptionContents = t("Hint to unlock: *{{hint}}*", {
        hint: storyline.hintDescription(state),
      });
    }
    if (isCompleted) {
      descriptionContents = t(
        "You have completed this Storyline, but not all of its bonuses are active. Retire to obtain the rest!",
      );
    }
    if (isBonusActive) {
      descriptionContents = storyline.getBonusDescription(state);
    }
    return (
      <View style={styles.descriptionTextContainer}>
        {isBonusActive ? (
          <View style={styles.effectContainer}>
            <Text>{t("Storyline completed!")}</Text>
            <Markdown>
              {t("Bonus: **{{description}}**", {
                description: descriptionContents,
              })}
            </Markdown>
          </View>
        ) : (
          <DescriptionViewer style={styles.descriptionViewer}>
            {descriptionContents}
          </DescriptionViewer>
        )}
      </View>
    );
  }, [
    storyline.getBonusDescription(state),
    isCompleted,
    isBonusActive,
    getElementLevel(state, SpellElement.Mind),
    isHintUnlockable,
    isHintRevealed,
    storyline.hintDescription(state),
  ]);

  const theme = useTheme();

  const icon = useMemo(() => {
    let icon = (
      <MaterialIcons
        accessibilityLabel="Locked"
        name="lock"
        size={20}
        color={theme.colors.onSurfaceDisabled}
      />
    );
    if (isHintUnlockable) {
      icon = (
        <MaterialCommunityIcons
          accessibilityLabel="Discoverable"
          name="lock-open-variant"
          size={20}
          color={theme.colors.onBackground}
        />
      );
    }
    if (isHintRevealed) {
      icon = (
        <MaterialIcons
          accessibilityLabel="Discovered"
          name="more-horiz"
          size={20}
          color={theme.colors.onBackground}
        />
      );
    }
    if (isCompleted) {
      icon = (
        <MaterialIcons
          accessibilityLabel="Completed but Inactive"
          name="error-outline"
          size={21}
          color={theme.colors.onBackground}
        />
      );
    }
    if (isBonusActive) {
      icon = (
        <MaterialIcons
          accessibilityLabel="Completed"
          name="check"
          size={20}
          color={theme.colors.onBackground}
        />
      );
    }
    return icon;
  }, [isHintUnlockable, isHintRevealed, isCompleted, isBonusActive, theme]);

  return useMemo(() => {
    return (
      <View style={styles.storylineContainer} key={storyline.name}>
        <TouchableWithoutFeedback
          accessibilityRole="button"
          accessibilityHint="Expand"
          onPress={props.onPressExpand}
        >
          <View style={styles.topRowContainer}>
            {icon}
            <Text
              h3
              accessibilityRole="none"
              key={storyline.name}
              onPress={props.onPressExpand}
              style={[
                styles.topContainer,
                !isEnabled && styles.disabledStorylineText,
              ]}
            >
              {isCompleted || isBonusActive
                ? storyline.name
                : isHintRevealed
                ? t("{{storylineTeaser}}...", {
                    storylineTeaser: storyline.name.substring(0, 3),
                  })
                : t("???")}
            </Text>
            <View style={styles.expandContainer}>
              <Entypo
                name={props.collapsed ? "chevron-right" : "chevron-down"}
                size={24}
                color={theme.colors.onBackground}
              />
            </View>
          </View>
        </TouchableWithoutFeedback>
        {!props.collapsed && <View>{descriptionContainer}</View>}
      </View>
    );
  }, [
    storyline.name,
    isEnabled,
    isCompleted,
    isBonusActive,
    icon,
    theme,
    !props.collapsed && descriptionContainer,
    props.collapsed,
    props.onPressExpand,
  ]);
}

const styles = StyleSheet.create({
  storylineContainer: { marginVertical: 8 },
  topRowContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    cursor: "pointer",
  },
  expandContainer: {
    width: 40,
  },
  descriptionTextContainer: {
    marginBottom: 8,
    marginTop: 8,
    flex: 1,
    flexDirection: "row",
    flexWrap: "wrap",
  },
  descriptionViewer: { flex: 1, justifyContent: "center" },
  effectContainer: {
    flex: 1,
  },
  effectSubcontainer: {
    flexDirection: "row",
    alignItems: "flex-start",
    flexWrap: "wrap",
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  topContainer: {
    flex: 1,
    marginLeft: 8,
  },
  disabledStorylineText: {
    color: "#aaa",
  },
});
