import { keysOf } from "../../../utils/CoreUtils";
import { formatNumber, formatTime } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource, getResourceAmount, grantResource } from "../../Resources";
import { grantElementExp } from "../../spells/Elements";
import {
  BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER,
  getBaseResearchRate,
} from "../../study/Study";
import { recordTimePieceConsumption } from "../../timetick/TimePieces";
import { applyTransformations } from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Action, ActionEffect, DoActionArgs } from "../Action";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../ActionAreas";

class UseTimePiecesStudy extends Action {
  getId(): string {
    return "useTimePiecesStudy";
  }

  getDisplayName(state: GameState): string {
    return t("Use Time Pieces (Study)");
  }

  isVisible(state: GameState): boolean {
    return (
      getResourceAmount(state, Resource.TimePiece) > 0 &&
      hasFlag(state, Flag.StudyUnlocked)
    );
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return {
      HOME: [ActionSubcategories.TIME_PIECES],
      STUDY: [ActionSubcategories.TIME_PIECES],
    };
  }

  getDisplayDescription(): string {
    return "Use your time pieces to fast-forward Studying.";
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      timePiecesPerResearchSecond: {
        value: 2,
        tags: [TransformationTags.TimePiecePerStudy],
      },
    };
  }

  getDisplayEffect(state: GameState): string {
    const timePiecesPerResearchSecond =
      this.getActionEffects(state).timePiecesPerResearchSecond;
    const secs = 3000 / timePiecesPerResearchSecond;
    const timeString = formatTime(secs, {
      shortForm: true,
      showDecimals: true,
    });
    const expPerResearcherPerSec = getBaseResearchRate(state);
    const transformedSecs = applyTransformations(
      [TransformationTags.GameSpeed],
      state,
      secs,
      { isWarp: true },
    );
    const expPerResearcher = expPerResearcherPerSec * transformedSecs;
    return t(
      `Instantly obtain {{timeString}} (Real World time) of Study ({{exp}} exp per researcher)`,
      {
        timeString: timeString,
        exp: formatNumber(expPerResearcher),
      },
    );
  }

  canAfford(state: GameState): boolean {
    return (
      super.canAfford(state) &&
      getResourceAmount(state, Resource.TimePiece) >= 3000
    );
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        TimePiece: 3000,
      },
      items: {},
    };
  }

  isEnabled(state: GameState): boolean {
    return (
      super.isEnabled(state) &&
      keysOf(state.run.studyAllocation).some(
        (element) => (state.run.studyAllocation[element] ?? 0) > 0,
      )
    );
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }

    const timePiecesPerResearchSecond =
      this.getActionEffects(state).timePiecesPerResearchSecond;
    const timePieces = this.getCost(state).resources[Resource.TimePiece] ?? 0;
    const secs = timePieces / timePiecesPerResearchSecond;
    const transformedSecs = applyTransformations(
      [TransformationTags.GameSpeed],
      state,
      secs,
      { isWarp: true },
    );

    state = grantResource(Resource.TimePiece, -timePieces)(state);
    state = recordTimePieceConsumption(state, timePieces);

    keysOf(state.run.studyAllocation).forEach((element) => {
      const researchersAllocated = state.run.studyAllocation[element] ?? 0;
      if (researchersAllocated > 0) {
        const expPerResearcherPerSec = applyTransformations(
          [element, TransformationTags.Study, TransformationTags.ExpGain],
          state,
          BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER,
          { isWarp: true, fromResearch: true },
        );
        state = grantElementExp(
          element,
          researchersAllocated * expPerResearcherPerSec * transformedSecs,
        )(state);
      }
    });

    return state;
  }
}

export default new UseTimePiecesStudy();
