import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import { isAutocastUnlocked } from "../../backend/autocast/Autocast";
import { getConcatenatedRequirements } from "../../backend/events/progression/LevelUp";
import { getTotalAmountOfItem } from "../../backend/items/Inventory";
import { TransmutationSpell } from "../../backend/items/transmute/TransmutationSpell";
import { translateElement } from "../../backend/spells/Elements";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import { CostViewer } from "../utility/CostViewer";
import { Markdown } from "../utility/Markdown";
import { SpellWizardsInfo } from "../utility/SpellWizardsInfo";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";
import { ItemInfo } from "./ItemInfo";

export interface TransmuteSpellDetailsProps {
  spell: TransmutationSpell;
}

export function TransmuteSpellInfo(props: TransmuteSpellDetailsProps) {
  const state = useGameState();

  const spell = props.spell;
  const item = spell.getItem();
  const params = item.getDefaultParams();
  const totalOwnedText = t(`Total owned: {{amount}}`, {
    amount: getTotalAmountOfItem(item, state),
  });
  const cost = spell.getCraftingMaterials(state);
  const isCraftable = spell.isVisible(state);

  const theme = useTheme();

  const concatenatedRequirements = getConcatenatedRequirements(spell);
  const cannotCastText = t(
    `You do not meet the requirements to cast this Transmutation spell. You need: {{requirements}}`,
    { requirements: concatenatedRequirements },
  );

  const exp = spell.getMainExp(state);

  const expGainText =
    exp.element != null
      ? t("Exp gain: +{{amount}} {{element}} exp", {
          amount: formatNumber(exp.expMax),
          element: translateElement(exp.element),
        })
      : t("Exp gain: +{{min}}~{{max}} exp (various)", {
          min: formatNumber(exp.expMin),
          max: formatNumber(exp.expMax),
        });

  const itemStack = useMemo(
    () => ({
      itemOccurrence: { itemId: item.getId(), params },
      amount: 1,
    }),
    [item.getId()],
  );

  return useMemo(
    () => (
      <View>
        <ItemInfo itemStack={itemStack} showTile={false} />
        <Markdown>{totalOwnedText}</Markdown>
        <Markdown>{expGainText}</Markdown>
        <View style={styles.costContainer}>
          <View style={styles.costTextContainer}>
            <Markdown>{t("Cost:")}</Markdown>
          </View>
          <CostViewer resources={cost.resources} items={cost.items} />
        </View>
        {!isCraftable && (
          <View style={styles.actionRow}>
            <Text
              style={[styles.cannotCraftText, { color: theme.colors.error }]}
            >
              {cannotCastText}
            </Text>
          </View>
        )}
      </View>
    ),
    [
      itemStack,
      totalOwnedText,
      expGainText,
      JSON.stringify(cost),
      isCraftable,
      cannotCastText,
      theme,
    ],
  );
}

export function TransmuteSpellDetails(props: TransmuteSpellDetailsProps) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const spell = props.spell;
  const item = spell.getItem();
  const params = item.getDefaultParams();
  const totalOwnedText = t(`Total owned: {{amount}}`, {
    amount: getTotalAmountOfItem(item, state),
  });

  const cost = spell.getCraftingMaterials(state);
  const canTransmute = spell.canCraft(state);
  const isCraftable = spell.isVisible(state);
  const theme = useTheme();

  const concatenatedRequirements = getConcatenatedRequirements(spell);
  const cannotCastText = t(
    `You do not meet the requirements to cast this Transmutation spell. You need: {{requirements}}`,
    { requirements: concatenatedRequirements },
  );

  const exp = spell.getMainExp(state);

  const expGainText =
    exp.element != null
      ? t("Exp gain: +{{amount}} {{element}} exp", {
          amount: formatNumber(exp.expMax),
          element: translateElement(exp.element),
        })
      : t("Exp gain: +{{min}}~{{max}} exp (various)", {
          min: formatNumber(exp.expMin),
          max: formatNumber(exp.expMax),
        });

  const maxCraftAmount = spell.timesCraftable(state);

  const transmuteCallback = useCallback(() => {
    stateRef.current.apply(spell.doAction.bind(spell, {}));
  }, [item]);

  const transmuteMaxCallback = useCallback(() => {
    stateRef.current.apply(spell.craftMax.bind(spell));
  }, [item]);

  const transmuteIntermediateCallback = useCallback(() => {
    stateRef.current.apply(spell.craftIntermediate.bind(spell));
  }, [item]);

  const transmuteMaxVisible = maxCraftAmount > 1;

  const transmuteIntermediateVisible =
    spell.isCraftIntermediatePotentiallyPossible(state);
  const transmuteIntermediateEnabled = spell.isCraftIntermediatePossible(state);

  const itemStack = useMemo(
    () => ({
      itemOccurrence: { itemId: item.getId(), params },
      amount: 1,
    }),
    [item.getId()],
  );

  return useMemo(
    () => (
      <View style={styles.craftingDetailsContainer}>
        <ItemInfo itemStack={itemStack} showTile={true} />
        <Markdown>{totalOwnedText}</Markdown>
        <Markdown>{expGainText}</Markdown>
        <View style={styles.costContainer}>
          <Markdown>{t("Cost:")}</Markdown>
          <CostViewer resources={cost.resources} items={cost.items} />
        </View>
        {isAutocastUnlocked(state) && (
          <View style={Styles.mt4}>
            <SpellWizardsInfo spellId={spell.getId()} />
          </View>
        )}
        <View style={styles.actionRow}>
          {isCraftable ? (
            [
              <Button
                key="craft_button"
                enableAutoRepress={true}
                autoRepressConfig={{
                  dropoffMultiplier: 0.8,
                  startingThreshold: 300,
                  startingRepressTime: 200,
                  minTimeMs: 50,
                }}
                disabled={!canTransmute}
                onPress={transmuteCallback}
              >
                {t("Transmute")}
              </Button>,
              transmuteMaxVisible && (
                <Button
                  key="craft_max"
                  disabled={!canTransmute}
                  onPress={transmuteMaxCallback}
                >
                  {t("Transmute All ({{amount}})", {
                    amount: formatNumber(maxCraftAmount),
                  })}
                </Button>
              ),
              transmuteIntermediateVisible && (
                <Button
                  key="craft_intermediate"
                  onPress={transmuteIntermediateCallback}
                  disabled={!transmuteIntermediateEnabled}
                >
                  {t("Transmute Materials", {
                    amount: formatNumber(maxCraftAmount),
                  })}
                </Button>
              ),
            ]
          ) : (
            <Text
              style={[styles.cannotCraftText, { color: theme.colors.error }]}
            >
              {cannotCastText}
            </Text>
          )}
        </View>
      </View>
    ),
    [
      itemStack,
      totalOwnedText,
      expGainText,
      JSON.stringify(cost.resources),
      JSON.stringify(cost.items),
      isAutocastUnlocked(state),
      canTransmute,
      transmuteMaxVisible,
      transmuteMaxCallback,
      maxCraftAmount,
      stateRef,
      theme,
      isCraftable,
      transmuteIntermediateCallback,
      transmuteIntermediateEnabled,
      transmuteIntermediateVisible,
    ],
  );
}

const styles = StyleSheet.create({
  craftingDetailsContainer: {
    marginBottom: 16,
  },
  tagRow: {
    marginVertical: 4,
  },
  actionRow: {
    flexDirection: "row",
    flexWrap: "wrap",
    marginTop: 8,
    gap: 8,
  },
  actionButtonContainer: {
    marginRight: 8,
  },
  costContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start",
    marginTop: 4,
  },
  costTextContainer: {
    marginRight: 8,
  },
  inventoryFullContainer: {
    flex: 1,
    marginHorizontal: 8,
  },
  cannotCraftText: {
    fontStyle: "italic",
  },
});
