import memoize from "sonic-memoize";
import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { CombatActionResultListeners } from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import { SpellElement } from "../spells/Elements";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  clearTemporaryEffect,
  getTemporaryEffectEndTime,
  grantTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Frozen extends TemporaryEffect {
  getId(): string {
    return "frozen";
  }
  getDisplayName(): string {
    return t("Frozen");
  }
  isBeneficial(): boolean {
    return false;
  }
  getIcon() {
    return require("./frozen.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "Attack gauge does not increase; next Fire attack received is reduced to 0",
    );
  }
}

export const frozen = new Frozen();

class FrozenProtection extends TemporaryEffect {
  getId(): string {
    return "frozenProtection";
  }
  getDisplayName(): string {
    return t("Frozen Protection");
  }
  isBeneficial(): boolean {
    return true;
  }
  getIcon() {
    return require("./frozenProtection.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t('Immune to "Frozen"');
  }
}

export const frozenProtection = new FrozenProtection();

const postFrozenProtectionDurationTags = memoize((target) => [
  target,
  TransformationTags.PostFrozenProtectionDuration,
]);

export function loadFrozen() {
  TemporaryEffects.register(frozen);
  TemporaryEffects.register(frozenProtection);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "frozen",
    frozen.getDisplayName(),
    TransformationType.Override,
    (state, params) => {
      return hasTemporaryEffect(state, frozen.getId(), params.target) &&
        (params.tags ?? []).includes(SpellElement.Fire)
        ? 0
        : undefined;
    },
  );

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.AttackDelay, target]],
      "frozen" + target,
      frozen.getDisplayName(),
      TransformationType.Override,
      (state, params) => {
        return hasTemporaryEffect(state, frozen.getId(), target)
          ? Infinity
          : undefined;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    frozen.getId(),
    CombatStat.AttackDelay,
  );

  CombatActionResultListeners.register((state, result) => {
    if (
      result.tags.includes(SpellElement.Fire) &&
      hasTemporaryEffect(state, frozen.getId(), result.target)
    ) {
      state = clearTemporaryEffect(frozen.getId(), result.target)(state);
    }
    return state;
  });

  registerTemporaryEffectListener(
    "frozenClearer",
    (newState, temporaryEffect) => {
      if (
        temporaryEffect.id === frozen.getId() &&
        temporaryEffect.endTime > temporaryEffect.setTime
      ) {
        // Someone's been frozen, but does it have temporary immunity?
        if (
          hasTemporaryEffect(
            newState,
            frozenProtection.getId(),
            temporaryEffect.target,
          )
        ) {
          temporaryEffect.endTime = getTemporaryEffectEndTime(
            newState,
            frozen.getId(),
            temporaryEffect.target,
          );
          return temporaryEffect;
        }

        // If no immunity, we should grant the immunity
        newState = grantTemporaryEffect(
          frozenProtection.getId(),
          applyTransformations(
            postFrozenProtectionDurationTags(temporaryEffect.target),
            newState,
            3.0,
          ) +
            temporaryEffect.endTime -
            getSecondsPlayed(newState),
          { mode: "add", target: temporaryEffect.target },
        )(newState);
      }
      return temporaryEffect;
    },
    true,
  );
}
