import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { ActionEffect } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { AttackTarget } from "../../exploration/AttackTarget";
import { getLastQueuedActionResult } from "../../exploration/ExplorationUtils";
import { Seared } from "../../temporaryeffects/Seared";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../temporaryeffects/TemporaryEffects";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";
import {
  getStandardAttackEffectText,
  getStandardSpellAttackEffect,
} from "../Utils";

class Sear extends CombatSpellBase {
  getId(): string {
    return "sear";
  }

  getSpellName(): string {
    return t("Sear");
  }

  getElement(): SpellElement {
    return SpellElement.Fire;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.ATTACK;
  }

  getExtraTags(): string[] {
    return [TransformationTags.AttackSpell];
  }

  getBaseManaCost(state: GameState): number {
    return 1050;
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Burn the enemy for a prolonged period of time, lowering their defenses.",
    );
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      average: {
        value: 170,
        tags: [TransformationTags.ActionMagnitude],
        unit: ":attack:",
      },
      variance: {
        value: 15,
        tags: [TransformationTags.ActionVariance],
        unit: ":attack:",
      },
      duration: {
        value: 8,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    const damageText = getStandardAttackEffectText(
      effects.average,
      effects.variance,
      explanations.average,
    );
    const effectExplanation = Seared.getParameterlessDisplayDescription(state);
    return t(
      '{{damageText}}; On hit, inflicts ^"Seared"^<{{effectExplanation}}> for ^{{duration}}^<{{durationExplanation}}> sec',
      {
        damageText,
        duration: formatNumber(effects.duration),
        durationExplanation: explanations.duration,
        effectExplanation,
      },
    );
  }

  doSpellAction(state: GameState): GameState {
    const effects = this.getActionEffects(state, "duration");
    state = getStandardSpellAttackEffect(state, this);
    if (getLastQueuedActionResult(state).isHit) {
      state = grantTemporaryEffect(Seared.getId(), effects.duration, {
        target: AttackTarget.Enemy,
      })(state);
    }
    return state;
  }

  getBaseCooldown(): number {
    return 7.0;
  }

  getBaseAutocastPointsRequirement(): number {
    return 30;
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Fire: 13,
    };
  }

  isActive(state: GameState): boolean {
    return hasTemporaryEffect(state, Seared.getId(), AttackTarget.Enemy);
  }

  allowRegisteringTemporaryEffectsAsPermacast(): boolean {
    return false;
  }
}

export default new Sear();
