function ConditionRow(props: { condition: AutocastConditionData }) {
  const condition = getConditionFromData(props.condition);
  const state = useGameState();
  const explanation = condition.getDisplayExplanation(
    state,
    props.condition.values,
  );
  return useMemo(() => <Markdown>{explanation}</Markdown>, [explanation]);
}

function PermaCastRow() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const theme = useTheme();
  const strategy = getCurrentAutocastStrategy(state);

  const onPressPlus = useCallback(
    () => stateRef.current.apply((state) => assignWizardsToPermaCast(state, 1)),
    [],
  );
  const onPressMinus = useCallback(
    () =>
      stateRef.current.apply((state) => assignWizardsToPermaCast(state, -1)),
    [],
  );

  const hasPermaCastSpells = keysOf(strategy.permaCastRules).length > 0;
  const permaCastSpells = cacheByArbitraryVersion(
    state,
    "permaCastSpellListSorted",
    (state) => getSortedPermaCastSpellList(state, strategy),
    (state) => Math.floor(Date.now() / 500),
  );

  const permaCastSpellList =
    permaCastSpells.length == 0
      ? t(
          "No spells yet! You can assign spells to Perma-Cast by opening a spell's details in the Spell menu.",
        )
      : t(
          "{{number}} spell(s), most expensive by :mana: and :wizardpower:: {{actualSpellList}}",
          {
            number: permaCastSpells.length,
            actualSpellList: permaCastSpells
              .slice(-5)
              .reverse()
              .map((spell) => spell.getBasicDisplayName(state))
              .join(t(", ")),
          },
        );

  const powerNeededPerSec = estimateWizardPowerConsumptionPerSecPermaCast(
    state,
    strategy,
  );
  const manaNeededPerSec = estimateManaConsumptionPerSecPermaCast(
    state,
    strategy,
  );
  const wizardsNeeded = estimateWizardsNeededPermaCast(state, strategy);
  const manaIncome = calculateIncomePerSecond(state).Mana ?? 0;
  const maxManaCost = Math.max(
    ...permaCastSpells.map((spell) => spell.getManaCost(state)),
  );
  const manaCap = getResourceCap(state, Resource.Mana);

  const warningWizardPower = useMemo(() => {
    let warningWizardPower = null;
    if (strategy.wizardsAssignedToPermaCast < wizardsNeeded) {
      warningWizardPower = (
        <View>
          <Markdown style={{ color: theme.colors.error }}>
            {t(
              "*Warning: you do not have enough wizards assigned to Perma-Cast! Some of your Perma-Cast spells may not actually get cast.*",
            )}
          </Markdown>
        </View>
      );
    }
    return warningWizardPower;
  }, [wizardsNeeded, strategy.wizardsAssignedToPermaCast]);
  const warningManaConsumption = useMemo(() => {
    let warningManaConsumption = null;
    if (manaIncome < manaNeededPerSec) {
      warningManaConsumption = (
        <View>
          <Markdown style={{ color: theme.colors.error }}>
            {t(
              "*Warning: your :mana: income is not enough to support all your Perma-Cast spells! Some of your Perma-Cast spells may not actually get cast, and your :mana: may not increase.*",
            )}
          </Markdown>
        </View>
      );
    }
    return warningManaConsumption;
  }, [manaIncome, manaNeededPerSec]);
  const warningManaCap = useMemo(() => {
    let warningManaCap = null;
    if (manaCap < maxManaCost) {
      warningManaCap = (
        <View>
          <Markdown style={{ color: theme.colors.error }}>
            {t(
              "*Warning: your Max:mana: is not high enough to cast at least one of your Perma-Cast spells! Those spells will not get cast, but they will also not consume :mana: or :wizardpower:.*",
            )}
          </Markdown>
        </View>
      );
    }
    return warningManaCap;
  }, [manaCap, maxManaCost]);

  const helperText = useMemo(
    () =>
      hasPermaCastSpells
        ? t(
            `*({{wizardPower}}:wizardpower:/s ({{wiz}} Wizards); {{mana}}:mana:/s)*`,
            {
              wizardPower: formatNumber(powerNeededPerSec, {
                showDecimals: true,
              }),
              wiz: formatNumber(wizardsNeeded),
              mana: formatNumber(manaNeededPerSec, { showDecimals: true }),
            },
          )
        : "",
    [hasPermaCastSpells, powerNeededPerSec, wizardsNeeded, manaNeededPerSec],
  );
  const topHalf = useMemo(
    () => (
      <View style={Styles.flexRowAlignCenter}>
        <View style={Styles.flex}>
          <Markdown>
            {t("***Perma-Cast spells:** {{permaCastSpellList}}*", {
              permaCastSpellList,
            })}
          </Markdown>
        </View>
      </View>
    ),
    [permaCastSpellList],
  );
  const explanationComponent = useMemo(
    () => (
      <View style={Styles.flexRowAlignCenter}>
        {helperText != "" ? <Markdown>{helperText}</Markdown> : null}
      </View>
    ),
    [helperText],
  );

  const warningComponent = useMemo(
    () => (
      <View>
        {warningWizardPower}
        {warningManaConsumption}
        {warningManaCap}
      </View>
    ),
    [warningWizardPower, warningManaConsumption, warningManaCap],
  );

  const viewStyle = useMemo(
    () => [
      Styles.flexRowAlignCenter,
      Styles.flex,
      Styles.mv4,
      Styles.p8,
      {
        backgroundColor: !hasPermaCastSpells
          ? theme.colors.surfaceDisabled
          : theme.colors.secondaryContainer,
        borderRadius: 8,
      },
    ],
    [hasPermaCastSpells, theme],
  );
  return useMemo(
    () => (
      <View style={viewStyle}>
        <View style={Styles.flex}>
          {topHalf}
          {explanationComponent}
          {warningComponent}
        </View>
        <View style={[Styles.flexRowAlignCenter, Styles.ml16]}>
          <View style={Styles.mr4}>
            <Text>{strategy.wizardsAssignedToPermaCast}</Text>
          </View>
          <PlusMinusAllocator
            disableMinus={strategy.wizardsAssignedToPermaCast <= 0}
            disablePlus={getFreeWizards(state) <= 0}
            onPressPlus={onPressPlus}
            onPressMinus={onPressMinus}
          />
        </View>
      </View>
    ),
    [
      topHalf,
      explanationComponent,
      warningComponent,
      strategy.wizardsAssignedToPermaCast,
      getFreeWizards(state),
      onPressPlus,
      onPressMinus,
      viewStyle,
    ],
  );
}

function AutocastRuleRow(props: {
  ruleId: string;
  // setEditRow: (ruleId: string) => void;
  toggleDisabled: (ruleId: string) => void;
  onPressMinus: (ruleId: string) => void;
  onPressPlus: (ruleId: string) => void;
  onDelete: (ruleId: string) => void;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const { ruleId, onPressMinus, onPressPlus, toggleDisabled, onDelete } = props;
  const rule = getAutocastRuleById(state, ruleId);
  const ruleStatus = getAutocastStatusByRule(state, rule);
  const spell = Spells.getByIdNullable(rule?.spellId);
  const wizardPointCap = spell?.getAutocastPointsRequirement(state);
  const manaCost = spell?.getCost(state)?.resources[Resource.Mana];
  const isSpellVisible = spell?.isVisible(state);
  const intervalBetweenCasts = estimateIntervalBetweenCastsForRule(
    state,
    rule,
    ruleStatus,
  );
  const manaCostPerSec = estimateManaConsumptionPerSecPerRule(
    state,
    rule,
    ruleStatus,
  );
  const hideRule = useCallback(
    () => stateRef.current.apply((s) => toggleAutocastRuleHidden(s, rule)),
    [rule],
  );
  const helperText = useMemo(
    () =>
      ruleStatus.wizardsAssigned > 0 && !ruleStatus.disabled
        ? t(`*({{secs}}s/cast; {{mana}}:mana:/s)*`, {
            secs: formatNumber(intervalBetweenCasts, { showDecimals: true }),
            mana: formatNumber(manaCostPerSec, { showDecimals: true }),
          })
        : "",
    [
      intervalBetweenCasts,
      ruleStatus.wizardsAssigned,
      ruleStatus.disabled,
      manaCostPerSec,
    ],
  );
  const wizardPointStatusComponent = useMemo(
    () => (
      <View style={[Styles.flexRowAlignCenter, Styles.mr8, { marginTop: 2 }]}>
        <GameIcon icon="wizardpower" small={true} style={Styles.mr4} />
        <Text>
          {t("{{amount}}/{{cap}}", {
            amount: formatNumber(ruleStatus.currentPoints),
            cap: formatNumber(wizardPointCap) ?? 0,
          })}
        </Text>
      </View>
    ),
    [formatNumber(ruleStatus.currentPoints), wizardPointCap],
  );
  const topHalf = useMemo(
    () => (
      <View style={Styles.flexRowAlignCenter}>
        <View style={[Styles.flex, { marginTop: -4 }]}>
          {rule.spellId == "checkOnly" ? (
            <Markdown>
              {t("Stop processing rules below if conditions not met:")}
            </Markdown>
          ) : !isSpellVisible ? (
            <Markdown>{t("[Rule hidden as spell is not unlocked]")}</Markdown>
          ) : rule.conditions.length == 0 ? (
            <Markdown>
              {t("*{{spellName}}*", {
                spellName: nullthrows(spell).getBasicDisplayName(state),
              })}
            </Markdown>
          ) : (
            <Markdown>
              {t("*{{spellName}}*) if:", {
                spellName: nullthrows(spell).getBasicDisplayName(state),
              })}
            </Markdown>
          )}
          {rule.conditions.map((condition, idx) => (
            <ConditionRow condition={condition} key={idx} />
          ))}
        </View>
        {spell != null && (
          <View style={[Styles.flexRowAlignCenter, Styles.ml8]}>
            <View style={Styles.mr4}>
              <Text>{ruleStatus.wizardsAssigned}</Text>
            </View>
            <PlusMinusAllocator
              disableMinus={!isSpellVisible || ruleStatus.wizardsAssigned <= 0}
              disablePlus={!isSpellVisible || getFreeWizards(state) <= 0}
              onPressPlus={onPressPlus.bind(null, ruleId)}
              onPressMinus={onPressMinus.bind(null, ruleId)}
            />
          </View>
        )}
      </View>
    ),
    [
      ruleId,
      JSON.stringify(rule.conditions),
      isSpellVisible,
      spell != null && spell.getBasicDisplayName(state),
      manaCost,
      ruleStatus.wizardsAssigned,
      getFreeWizards(state),
      onPressMinus,
      onPressPlus,
    ],
  );
  const rightSide = useMemo(
    () => (
      <View
        style={[
          Styles.ml4,
          Styles.mr4,
          {
            alignSelf: "stretch",
            alignItems: "center",
            justifyContent: "space-around",
          },
        ]}
      >
        <Switch
          value={!ruleStatus.disabled}
          onValueChange={toggleDisabled.bind(null, ruleId)}
        />
        <View style={Styles.flexRowAlignCenter}>
          <IconButton
            icon={(props) => (
              <MaterialIcons name="remove" size={20} color={props.color} />
            )}
            style={[{ margin: 0, width: 20, height: 20 }, Styles.mr4]}
            onPress={hideRule}
          />
          <IconButton
            icon={(props) => (
              <MaterialIcons name="close" size={20} color={props.color} />
            )}
            style={{ margin: 0, width: 20, height: 20 }}
            onPress={() => onDelete(ruleId)}
          />
        </View>
      </View>
    ),
    [ruleStatus.disabled, toggleDisabled, ruleId, onDelete, hideRule],
  );
  const explanationComponent = useMemo(
    () =>
      spell != null &&
      isSpellVisible && (
        <View style={Styles.flexRowAlignCenter}>
          {wizardPointStatusComponent}
          <Markdown>{helperText}</Markdown>
        </View>
      ),
    [wizardPointStatusComponent, helperText, rule.spellId, isSpellVisible],
  );
  return useMemo(
    () => (
      <View style={[Styles.flexRowAlignCenter, Styles.flex]}>
        <View style={Styles.flex}>
          {topHalf}
          {explanationComponent}
        </View>
        {rightSide}
      </View>
    ),
    [topHalf, rightSide, explanationComponent],
  );
}

function AutocastRuleRowContainer(props: {
  params: RenderItemParams<AutocastRule>;
  // editRow: number;
  saveRule: (rule: AutocastRule) => void;
  deleteRule: (ruleId: string) => void;
  // setEditRow: (ruleId: string) => void;
  toggleDisabled: (ruleId: string) => void;
  onPressMinus: (ruleId: string) => void;
  onPressPlus: (ruleId: string) => void;
}) {
  const {
    params,
    // editRow,
    saveRule,
    deleteRule,
    // setEditRow,
    toggleDisabled,
    onPressMinus,
    onPressPlus,
  } = props;
  const rule = params.item;
  //const idx = params.getIndex();
  const theme = useTheme();
  const spell = Spells.getByIdNullable(rule.spellId);
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const ruleStatus = getAutocastStatusByRule(state, rule);
  const ruleDisabled =
    ruleStatus.disabled ||
    (spell != null && ruleStatus.wizardsAssigned <= 0) ||
    (spell != null && !spell.isVisible(state));

  const dragHandle = useMemo(
    () => <DragHandle drag={params.drag} />,
    [params.drag],
  );

  const isDark = theme.dark;
  const isHidden = ruleStatus.hidden;

  const surfaceColor =
    spell != null && spell.getElement() != null
      ? getThemeByElementAndDarkMode(spell.getElement(), isDark).colors
          .secondaryContainer
      : theme.colors.secondaryContainer;

  const viewStyle = useMemo(
    () => [
      Styles.flexRowAlignCenter,
      Styles.flex,
      Styles.mv4,
      Styles.p4,
      {
        backgroundColor: ruleDisabled
          ? theme.colors.surfaceDisabled
          : surfaceColor,
        borderRadius: 8,
      },
    ],
    [ruleDisabled, theme, surfaceColor],
  );

  const boundSave = useMemo(() => saveRule.bind(null), [saveRule]);
  const boundDelete = useMemo(
    () => deleteRule.bind(null, rule.id),
    [rule.id, deleteRule],
  );

  const unhideRule = useCallback(
    () => stateRef.current.apply((s) => toggleAutocastRuleHidden(s, rule)),
    [rule.id],
  );
  const unhideIconFn = useCallback(
    (props) => (
      <MaterialIcons name="unfold-more" size={20} color={props.color} />
    ),
    [],
  );

  const hiddenViewStyle = useMemo(
    () => [Styles.flexRowSpaceBetween, Styles.ph8],
    [],
  );
  const hiddenText = useMemo(
    () =>
      t("{{spellName}} (hidden){{wizardCount}}", {
        spellName: spell.getBasicDisplayName(state),
        wizardCount:
          ruleStatus.wizardsAssigned > 0
            ? t(" ({{wizardCount}})", {
                wizardCount: formatNumber(ruleStatus.wizardsAssigned),
              })
            : "",
      }),
    [spell.getDisplayName(state), ruleStatus.wizardsAssigned],
  );
  return useMemo(
    () =>
      !isHidden ? (
        <View style={viewStyle}>
          {dragHandle}
          <AutocastRuleRow
            ruleId={rule.id}
            // setEditRow={setEditRow}
            onPressMinus={onPressMinus}
            onPressPlus={onPressPlus}
            onDelete={deleteRule}
            toggleDisabled={toggleDisabled}
          />
        </View>
      ) : (
        <View style={hiddenViewStyle}>
          <Text key="hiddenText">{hiddenText}</Text>
          <IconButton
            key="unhide"
            icon={unhideIconFn}
            style={styles.unfoldButton}
            onPress={unhideRule}
          />
        </View>
      ),
    [
      isHidden,
      viewStyle,
      dragHandle,
      rule.id,
      rule,
      boundSave,
      boundDelete,
      // setEditRow,
      onPressMinus,
      onPressPlus,
      toggleDisabled,
      hiddenViewStyle,
      hiddenText,
      unhideIconFn,
      unhideRule,
    ],
  );
}

function WizardsBottomComponent(props) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const strategy = getCurrentAutocastStrategy(state);

  const theme = useTheme();

  const manaConsumption = estimateManaConsumptionPerSecPerStrategy(
    state,
    strategy,
  );
  const manaIncome = calculateIncomePerSecond(state)?.Mana ?? 0;

  const expGain = estimateExpGainPerSecPerStrategy(
    state,
    getCurrentAutocastStrategy(state),
  );
  let maxExpGain = 0,
    maxExpGainElement = SpellElement.Fire;
  for (let element of keysOf(expGain)) {
    if (expGain[element] > maxExpGain) {
      maxExpGainElement = element;
      maxExpGain = expGain[element];
    }
  }

  const wizardPowerProductionPerWizard =
    getAutocastPointIncomePerSecPerWizard(state);
  const wizardPowerProductionPerWizardExplanation =
    explainAutocastPointIncomePerSecPerWizard(state);

  const totalWizards = getWizardAmount(state);
  const explainTotalWizards = explainWizardAmount(state);

  const bottomComponent = useMemo(
    () => (
      <View
        style={[
          styles.loadoutsContainer,
          { borderColor: theme.colors.outlineVariant },
        ]}
      >
        <View style={[Styles.flexRowAlignCenter]}>
          <View style={[Styles.flex, Styles.mr8]}>
            <Markdown>{t("Wizards remaining:")}</Markdown>
          </View>
          <View>
            <Markdown>
              {t(
                "{{remainingWizards}}/^{{totalWizards}}^<{{explainTotalWizards}}>",
                {
                  remainingWizards: getFreeWizards(state),
                  totalWizards: totalWizards,
                  explainTotalWizards: explainTotalWizards,
                },
              )}
            </Markdown>
          </View>
        </View>
        <View style={[Styles.flexRowAlignCenter]}>
          <View style={[Styles.flex, Styles.mr8]}>
            <Markdown>{t("Wizard Power production per Wizard:")}</Markdown>
          </View>
          <View>
            <Markdown>
              {t("^{{amount}}^<{{explanation}}>:wizardpower:/sec", {
                amount: formatNumber(wizardPowerProductionPerWizard, {
                  showDecimals: true,
                }),
                explanation: wizardPowerProductionPerWizardExplanation,
              })}
            </Markdown>
          </View>
        </View>
        <View style={[Styles.flexRowAlignCenter]}>
          <View style={[Styles.flex, Styles.mr8]}>
            <Markdown>{t("Estimated Mana consumption:")}</Markdown>
          </View>
          <View>
            <Markdown
              style={
                manaConsumption > manaIncome && { color: theme.colors.error }
              }
            >
              {t("{{amount}}:mana:/sec", {
                amount: formatNumber(manaConsumption, { showDecimals: true }),
              })}
            </Markdown>
          </View>
        </View>
        <View style={[Styles.flexRowAlignCenter]}>
          <View style={[Styles.flex, Styles.mr8]}>
            <Markdown>{t("Estimated exp gain:")}</Markdown>
          </View>
          <View>
            <Markdown>
              {maxExpGain > 0
                ? t("{{amount}} {{mainElement}} + others / min", {
                    amount: formatNumber(maxExpGain * 60, {
                      showDecimals: true,
                    }),
                    mainElement: translateElement(maxExpGainElement),
                  })
                : t("-")}
            </Markdown>
          </View>
        </View>
      </View>
    ),
    [
      theme,
      getFreeWizards(state),
      totalWizards,
      explainTotalWizards,
      wizardPowerProductionPerWizard,
      wizardPowerProductionPerWizardExplanation,
      manaConsumption,
      manaIncome,
      maxExpGain,
      maxExpGainElement,
    ],
  );
  return bottomComponent;
}

function WizardsMainComponent(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const theme = useTheme();

  const strategy = getCurrentAutocastStrategy(state);

  // const [editRow, setEditRow] = useState<number | undefined>(undefined);

  const saveRule = useCallback((rule: AutocastRule) => {
    stateRef.current.apply((s) => {
      const currentStrategy = getCurrentAutocastStrategy(s);
      const idx = getAutocastRuleIndexByRule(state, rule);
      currentStrategy.rules[idx] = rule;
      s.run.autocast.rules[rule.id] = {
        currentPoints: 0,
        wizardsAssigned: rule.wizardsAssigned,
        disabled: rule.disabled,
        hidden: false,
      };
      return s;
    });
    // setEditRow(undefined);
  }, []);

  const deleteRule = useCallback((ruleId: string) => {
    stateRef.current.apply((s) => {
      const currentStrategy = getCurrentAutocastStrategy(s);
      currentStrategy.rules = currentStrategy.rules.filter(
        (rule) => rule.id != ruleId,
      );
      return s;
    });
    // setEditRow(undefined);
  }, []);

  const toggleDisabled = useCallback((ruleId: string) => {
    stateRef.current.apply((s) =>
      toggleAutocastRuleDisabled(s, getAutocastRuleById(s, ruleId)),
    );
  }, []);

  const onPressPlus = useCallback((ruleId: string) => {
    stateRef.current.apply((s) =>
      assignWizardsToRule(s, getAutocastRuleById(s, ruleId), 1),
    );
  }, []);

  const onPressMinus = useCallback((ruleId: string) => {
    stateRef.current.apply((s) =>
      assignWizardsToRule(s, getAutocastRuleById(s, ruleId), -1),
    );
  }, []);

  const renderItem: RenderItem<AutocastRule> = useCallback(
    (params: RenderItemParams<AutocastRule>) => (
      <AutocastRuleRowContainer
        params={params}
        saveRule={saveRule}
        deleteRule={deleteRule}
        // editRow={editRow}
        // setEditRow={setEditRow}
        onPressMinus={onPressMinus}
        onPressPlus={onPressPlus}
        toggleDisabled={toggleDisabled}
      />
    ),
    [
      saveRule,
      deleteRule,
      // editRow,
      // setEditRow,
      onPressMinus,
      onPressPlus,
      toggleDisabled,
    ],
  );
  const keyExtractor = useCallback(
    (rule: AutocastRule, idx: number) => rule.id,
    [],
  );

  const addNewRule = useCallback(() => {
    stateRef.current.apply((s) => {
      const currentStrategy = getCurrentAutocastStrategy(s);
      currentStrategy.rules.push({
        id: Math.random().toString(),
        disabled: true,
        wizardsAssigned: 0,
        spellId: ChannelFire.getId(),
        conditions: [],
      });
      s.global.autocast.currentStrategy = currentStrategy;
      return s;
    });
  }, []);

  const showPermaCast = permacastStoryline.isBonusActive(state);

  const rulesByAutocastCategory = useMemo(() => {
    const rulesByAutocastCategory = {};
    for (let rule of strategy.rules) {
      const category = Spells.getById(rule.spellId).getAutocastCategory();
      if (rulesByAutocastCategory[category] == null) {
        rulesByAutocastCategory[category] = [];
      }
      rulesByAutocastCategory[category].push(rule);
    }
    return rulesByAutocastCategory;
  }, [JSON.stringify(strategy.rules)]);

  const togglePause = useCallback(() => {
    stateRef.current.apply((state) => {
      state.run.autocast.autocastPaused = !state.run.autocast.autocastPaused;
      return state;
    });
  }, []);
  const paused = state.run.autocast.autocastPaused;

  const mainComponent = useMemo(
    () => (
      <NestableScrollContainer>
        {(keysOf(rulesByAutocastCategory).length > 0 || showPermaCast) && (
          <View style={[Styles.ph4, Styles.mt8]}>
            <CheckBox
              title={
                <View style={Styles.flex}>
                  <Text style={paused && { color: theme.colors.error }}>
                    {t("Pause all Wizards")}
                  </Text>
                </View>
              }
              checked={paused}
              onPress={togglePause}
            />
          </View>
        )}
        {showPermaCast ? (
          <View style={[Styles.ph4, Styles.mt8]}>
            <PermaCastRow />
          </View>
        ) : undefined}
        {keysOf(rulesByAutocastCategory).length == 0 && !showPermaCast && (
          <Markdown>
            {t(
              "*You have no rules currently set for your Wizards! You can tell them to cast spells for you from the Spell menu (by expanding a spell's details) or from the Transmute screen.*",
            )}
          </Markdown>
        )}
        {keysOf(rulesByAutocastCategory).map((category) => (
          <CategoryContainer
            key={category}
            title={translateSpellAutocastCategory(category)}
            containerStyle={Styles.ph4}
          >
            <NestableDraggableFlatList
              style={styles.container}
              containerStyle={[Styles.flex, styles.contentContainer]}
              // containerStyle={Styles.flex}
              // style={Styles.flex}
              data={rulesByAutocastCategory[category]}
              renderItem={renderItem}
              keyExtractor={keyExtractor}
              onDragEnd={(params) => {
                stateRef.current.apply((state) => {
                  const strategy = getCurrentAutocastStrategy(state);
                  const ruleIndices = [];
                  const ruleIds = params.data.map((rule) => rule.id);
                  for (let idx in strategy.rules) {
                    const rule = strategy.rules[idx];
                    if (ruleIds.includes(rule.id)) {
                      ruleIndices.push(idx);
                    }
                  }
                  for (let idx in params.data) {
                    const rule = params.data[idx];
                    strategy.rules[ruleIndices[idx]] = rule;
                  }
                  state.global.autocast.currentStrategy = strategy;
                  return state;
                });
              }}
            />
          </CategoryContainer>
        ))}
      </NestableScrollContainer>
    ),
    [
      renderItem,
      keyExtractor,
      showPermaCast,
      rulesByAutocastCategory,
      theme,
      paused,
      togglePause,
    ],
  );

  // const mainComponent = useMemo(
  //   () => (
  //     // <ScrollView
  //     //
  //     // >
  //     <DraggableFlatList
  //       style={styles.container}
  //       containerStyle={[Styles.flex, styles.contentContainer]}
  //       // containerStyle={Styles.flex}
  //       // style={Styles.flex}
  //       data={strategy.rules}
  //       renderItem={renderItem}
  //       keyExtractor={keyExtractor}
  //       ListHeaderComponent={showPermaCast ? <PermaCastRow /> : undefined}
  //       onDragEnd={(params) => {
  //         stateRef.current.apply((state) => {
  //           const strategy = getCurrentAutocastStrategy(state);
  //           strategy.rules = params.data;
  //           state.global.autocast.currentStrategy = strategy;
  //           return state;
  //         });
  //       }}
  //       ListEmptyComponent={
  //         !showPermaCast ? (
  //           <Markdown>
  //             {t(
  //               "*You have no rules currently set for your Wizards! You can tell them to cast spells for you from the Spell menu (by expanding a spell's details) or from the Transmute screen.*",
  //             )}
  //           </Markdown>
  //         ) : undefined
  //       }
  //     ></DraggableFlatList>
  //     // </ScrollView>
  //   ),
  //   [renderItem, keyExtractor, showPermaCast, JSON.stringify(strategy.rules)],
  // );
  return mainComponent;
}

export default function Wizards(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const [slotNumber, setSlotNumber] = useState<string>(
    state.global.autocast.lastStrategyIndex?.toString?.() ?? "0",
  );

  const loadoutAmount = 8;
  const loadoutsSlotPicker = useMemo(() => {
    const items = [];
    for (let i = 0; i < loadoutAmount; i++) {
      items.push({
        key: i.toString(),
        value: i.toString(),
        label: t("Slot {{idx}}", { idx: i + 1 }),
      });
    }
    return (
      <Picker
        onValueChange={setSlotNumber}
        selectedValue={slotNumber}
        disablePlaceholder={true}
        data={items}
      />
    );
  }, [loadoutAmount, setSlotNumber, slotNumber]);

  const loadoutObjects = state.global.autocast.savedStrategies;

  const [loadoutName, setLoadoutName] = useState("");

  useEffect(
    () =>
      setLoadoutName(
        slotNumber != null
          ? loadoutObjects?.[Number.parseInt(slotNumber)]?.name ?? ""
          : "",
      ),
    [slotNumber],
  );

  useEffect(() => {
    setSlotNumber(state.global.autocast.lastStrategyIndex?.toString?.() ?? "0");
  }, [state.global.autocast.lastStrategyIndex]);

  const showHeader = hasEverHadFlag(state, Flag.AssignedWizard);

  const headerComponent = useMemo(
    () => (
      <View style={Styles.ph4}>
        <CategoryContainer title={t("Strategies")}>
          <View key="strat" style={Styles.mt8}>
            <View key="strat">
              <View style={Styles.flexRowAlignCenter}>
                <View style={Styles.flex}>
                  <Text>{t("Save:")}</Text>
                </View>
                {loadoutsSlotPicker}
              </View>
              <View key="strat" style={[Styles.flexRowAlignCenter, Styles.mt4]}>
                <TextInput
                  key="strat"
                  label={t("Strategy name")}
                  value={loadoutName}
                  onChangeText={(text) => setLoadoutName(text)}
                  mode="outlined"
                  dense={true}
                  style={Styles.flex}
                  maxLength={20}
                />
                <View style={[Styles.ml8, Styles.flexRowAlignCenter]}>
                  <Button
                    compact={true}
                    onPress={() => {
                      stateRef.current.apply((state) =>
                        saveCurrentAutocastStrategy(
                          state,
                          loadoutName,
                          Number.parseInt(slotNumber),
                        ),
                      );
                    }}
                  >
                    {t("Save")}
                  </Button>
                  <Button
                    compact={true}
                    style={Styles.ml4}
                    onPress={() => {
                      setLoadoutName("");
                      stateRef.current.apply((state) =>
                        clearAutocastStrategy(
                          state,
                          Number.parseInt(slotNumber),
                        ),
                      );
                    }}
                  >
                    {t("Clear")}
                  </Button>
                </View>
              </View>
            </View>
            <View style={Styles.mt8}>
              <AutocastLoadoutLoadContainer />
            </View>
          </View>
        </CategoryContainer>
      </View>
    ),
    [slotNumber, loadoutName, setLoadoutName, loadoutsSlotPicker],
  );

  return useMemo(
    () => (
      <View style={styles.campusContainer}>
        {showHeader && headerComponent}
        <WizardsMainComponent />
        <WizardsBottomComponent />
      </View>
    ),
    [showHeader, headerComponent],
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
  },
  contentContainer: {
    paddingVertical: 8,
    alignItems: "stretch",
    justifyContent: "flex-start",
  },
  hintContents: {
    marginLeft: 16,
    marginTop: 8,
    paddingRight: 32,
  },
  hintContentsText: {
    fontStyle: "italic",
  },
  hintProgress: {
    marginTop: 4,
  },
  loadoutsContainer: {
    borderTopWidth: 1,
    paddingVertical: 8,
    paddingHorizontal: 4,
  },
  categoryTitle: {
    marginLeft: 8,
  },
  categoryContainer: {
    paddingTop: 4,
    marginBottom: 8,
  },
  campusContainer: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
  },
  campusHintWarning: {
    marginTop: 16,
  },
  unfoldButton: { margin: 0, width: 20, height: 20 },
});
