import { t } from "i18next";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { TextInput } from "react-native-paper";
import { Flag } from "../backend/Flag";
import { hasFlag } from "../backend/Flags";
import {
  addGameStateListener,
  clearGameStateListener,
} from "../backend/GameStateListeners";
import { isEnhancementUnlocked } from "../backend/enhancement/Enhancements";
import { isExplorationUnlocked } from "../backend/exploration/Exploration";
import { translateItemTag } from "../backend/items/Item";
import { ItemTagEnum } from "../backend/items/ItemTagEnum";
import {
  TransmutationSpell,
  getAllTransmutationSpellsEverCastable,
} from "../backend/items/transmute/TransmutationSpell";
import {
  getElementLevel,
  getUnlockedElements,
} from "../backend/spells/Elements";
import { Spells } from "../backend/spells/Spells";
import { fromEntries } from "../utils/CoreUtils";
import { TransmuteSpellDetails } from "./inventory/CraftingDetails";
import {
  ItemGrid,
  ItemGridElement,
  ItemGridElementUsage,
} from "./inventory/ItemGrid";
import { Button } from "./utility/Button";
import { CheckBox } from "./utility/CheckBox";
import { Picker as UtilityPicker } from "./utility/Picker";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { UIContext, useUIStateFromContext } from "./utility/UIContext";
import { VisibilityContext } from "./utility/VisibilityContext";
import { useGameState } from "./utility/useGameState";

export const ITEM_TAG_ORDER = [
  ItemTagEnum.Material,
  ItemTagEnum.Hand,
  ItemTagEnum.Body,
  ItemTagEnum.Accessory,
  ItemTagEnum.Consumable,
  ItemTagEnum.Equipment,
  ItemTagEnum.Familiar,
  ItemTagEnum.QuestItem,
];

export default function Transmute() {
  const state = useGameState();
  const uiContext = useContext(UIContext);
  const [selectedElement, setSelectedElement] = useState(
    null as ItemGridElement | null,
  );
  const [selectedTag, setSelectedTag] = useUIStateFromContext(
    uiContext,
    "CraftingTag",
    "All",
  );
  const maxElementLevels = state.world.maxElementLevels;
  const currentElementLevels = fromEntries(
    getUnlockedElements(state).map((element) => [
      element,
      getElementLevel(state, element),
    ]),
  );
  let castableSpells = useMemo(() => {
    const allItems = getAllTransmutationSpellsEverCastable(state);
    return allItems;
  }, [
    JSON.stringify(maxElementLevels),
    isExplorationUnlocked(state),
    isEnhancementUnlocked(state),
    hasFlag(state, Flag.DeepCavernsUnlocked),
    hasFlag(state, Flag.LostForestUnlocked),
  ]);

  const tags = useMemo(() => {
    const tagSet = new Set<string>();
    castableSpells.forEach((spell) => {
      const tags = spell.getItem().getTags();
      tags.forEach((tag) => tagSet.add(tag));
    });
    const tags = Array.from(tagSet).sort((a, b) => {
      const indexA = ITEM_TAG_ORDER.findIndex((tag) => tag == a);
      const indexB = ITEM_TAG_ORDER.findIndex((tag) => tag == b);
      return indexA - indexB;
    });
    return tags;
  }, [castableSpells]);

  useEffect(() => {
    if (!tags.includes(selectedTag)) {
      setSelectedTag("All");
    }
  }, [tags.join(), selectedTag]);

  useEffect(() => setSelectedElement(null), [selectedTag]);

  const selectedElementRef = useRef(selectedElement);
  selectedElementRef.current = selectedElement;

  const visible = useContext(VisibilityContext);

  useEffect(() => {
    if (visible) {
      addGameStateListener(
        "TransmutationComponent",
        (newState) => {
          setSelectedElement(null);
        },
        (state) =>
          getAllTransmutationSpellsEverCastable(state)
            .map((item) => item.getId())
            .join(),
      );
      return () => clearGameStateListener("TransmutationComponent");
    }
  }, [visible]);

  const onPressItem = React.useCallback(
    (item: ItemGridElement) => {
      setSelectedElement(item);
    },
    [setSelectedElement],
  );

  const tagButtons = useMemo(() => {
    return (
      <UtilityPicker
        onValueChange={setSelectedTag}
        style={styles.picker}
        selectedValue={selectedTag}
        data={["All", ...tags].map((tag) => ({
          label: tag == "All" ? t("All") : translateItemTag(tag as ItemTagEnum),
          value: tag,
          key: tag,
        }))}
        disablePlaceholder={true}
      />
    );
  }, [setSelectedTag, selectedTag, JSON.stringify(tags)]);

  const sortedCastableSpells = useMemo(
    () =>
      castableSpells
        .filter(
          (spell) =>
            selectedTag === "All" ||
            spell
              .getItem()
              .getTags()
              .includes(selectedTag as ItemTagEnum),
        )
        .sort((a, b) => {
          const firstTagA = ITEM_TAG_ORDER.findIndex((tag) =>
            a
              .getItem()
              .getTags()
              .includes(tag as ItemTagEnum),
          );
          const firstTagB = ITEM_TAG_ORDER.findIndex((tag) =>
            b
              .getItem()
              .getTags()
              .includes(tag as ItemTagEnum),
          );
          if (firstTagA != firstTagB) {
            return firstTagA - firstTagB;
          }
          const maxA = Math.max(
            ...(Object.values(a.getLevelRequirements()).filter(
              (n) => n != undefined,
            ) as number[]),
          );
          const maxB = Math.max(
            ...(Object.values(b.getLevelRequirements()).filter(
              (n) => n != undefined,
            ) as number[]),
          );
          return maxA - maxB;
        }),
    [selectedTag, castableSpells],
  );

  const [hideLocked, setHideLocked] = useUIStateFromContext(
    uiContext,
    "crafting_hide_locked",
    false,
  );

  const toggleLockedCallback = useCallback(() => {
    setHideLocked(!hideLocked);
  }, [hideLocked, setHideLocked]);

  useEffect(() => setSelectedElement(null), [hideLocked]);

  const [filterText, setFilterText] = useState("");
  useEffect(() => setSelectedElement(null), [filterText]);

  const [filterTextTemp, setFilterTextTemp] = useState("");

  useEffect(() => {
    const timeout = setTimeout(() => setFilterText(filterTextTemp), 400);
    return () => clearTimeout(timeout);
  }, [filterTextTemp]);

  const itemGridElements = useMemo(
    () =>
      sortedCastableSpells.map((spell) => ({
        itemOrSpellId: spell.getId(),
        amount: 1,
        params: spell.getItem().getDefaultParams(),
        disabled: !spell.isVisible(state),
      })),
    [
      JSON.stringify(maxElementLevels),
      JSON.stringify(currentElementLevels),
      sortedCastableSpells,
    ],
  );

  const itemGridElementsJSON = useMemo(
    () => JSON.stringify(itemGridElements),
    [itemGridElements],
  );

  const isAnyLocked = useMemo(() => {
    return itemGridElements.some((element) => element.disabled);
  }, [itemGridElementsJSON]);

  const itemGridElementsFiltered = useMemo(() => {
    let elements = itemGridElements;
    if (hideLocked) {
      elements = elements.filter(
        (itemGridElement) => !itemGridElement.disabled,
      );
    }
    if (filterText.length >= 3) {
      elements = elements.filter((itemGridElement) => {
        return Spells.getById(itemGridElement.itemOrSpellId)
          .getSpellName()
          .toLowerCase()
          .includes(filterText.toLowerCase());
      });
    }
    return elements.map((element, index) => ({
      ...element,
      disableTooltip: selectedElement?.itemOrSpellId == element.itemOrSpellId,
    }));
  }, [itemGridElementsJSON, hideLocked, selectedElement, filterText]);

  const itemGridElementsFilteredJSON = useMemo(
    () => JSON.stringify(itemGridElementsFiltered),
    [itemGridElementsFiltered],
  );

  const selectedSpell =
    selectedElement != null
      ? (Spells.getById(selectedElement?.itemOrSpellId) as TransmutationSpell)
      : undefined;

  const itemGrid = useMemo(
    () => (
      <ItemGrid
        items={itemGridElementsFiltered}
        location={ItemGridElementUsage.TRANSMUTE}
        onPressItem={onPressItem}
      />
    ),
    [itemGridElementsFilteredJSON, onPressItem],
  );

  const details = useMemo(
    () => (
      <View style={styles.detailsContainer}>
        {selectedSpell && <TransmuteSpellDetails spell={selectedSpell} />}
      </View>
    ),
    [selectedSpell?.getId()],
  );

  const clearFilter = useCallback(() => {
    setFilterText("");
    setFilterTextTemp("");
  }, []);

  return useMemo(
    () => (
      <View style={styles.container}>
        <View style={[Styles.flexRowSpaceBetween, Styles.mb8, Styles.ph8]}>
          <View style={[styles.tagPickerContainer]}>{tagButtons}</View>
          {isAnyLocked && (
            <CheckBox
              title={t("Hide locked")}
              checked={hideLocked}
              onPress={toggleLockedCallback}
              containerStyle={styles.checkboxContainer}
            />
          )}
        </View>
        <View style={[Styles.flexRowAlignCenter, Styles.mb8, Styles.ph8]}>
          <Text>{t("Filter:")}</Text>
          <TextInput
            style={[Styles.ml8, { minWidth: 180 }]}
            mode="outlined"
            dense={true}
            value={filterTextTemp}
            onChangeText={setFilterTextTemp}
          />
          {filterTextTemp.length > 0 && (
            <Button onPress={clearFilter} compact={true} style={Styles.ml8}>
              {t("Clear")}
            </Button>
          )}
        </View>
        {itemGrid}
        {details}
      </View>
    ),
    [
      tagButtons,
      isAnyLocked,
      hideLocked,
      toggleLockedCallback,
      itemGrid,
      details,
      filterTextTemp,
      setFilterTextTemp,
      clearFilter,
    ],
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
    paddingTop: 16,
  },
  detailsContainer: {
    marginTop: 16,
    paddingHorizontal: 8,
    paddingBottom: 8,
  },
  buttonGroupContainer: {
    flexDirection: "row",
    alignItems: "flex-start",
    height: undefined,
    flexWrap: "wrap",
    justifyContent: "flex-start",
    borderWidth: 0,
  },
  buttonGroupButtonContainer: {
    flex: 1,
    height: 40,
    borderWidth: 1,
    borderColor: "#eee",
    margin: 4,
  },
  picker: {
    flex: 1,
  },
  tagPickerContainer: {
    width: Platform.OS != "web" ? 180 : null,
    ...Platform.select({
      android: { backgroundColor: "#eee", borderRadius: 10, default: {} },
    }),
  },
  row: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    flexWrap: "wrap",
    marginBottom: 16,
    paddingHorizontal: 8,
  },
  checkboxContainer: {
    marginLeft: 0,
    padding: 0,
    maxWidth: 140,
  },
  checkboxText: {
    fontWeight: "normal",
  },
});
