import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { StyleSheet, TouchableOpacity, View } from "react-native";
import { ScrollView } from "react-native-gesture-handler";
import { useTheme } from "react-native-paper";
import { Flag } from "../backend/Flag";
import { hasWorldFlag } from "../backend/Flags";
import {
  SpellElement,
  getFullyUnlockedElements,
  translateElement,
} from "../backend/spells/Elements";
import {
  SYNCHRO_COLS,
  SYNCHRO_ROWS,
  calculateMaxOccurrencesInSynchroGrid,
  getCurrentSynchroStrategy,
  getSortedSynchroBonuses,
  getSynchroPosition,
  getTotalOccurrencesInSynchroGrid,
  hasNegativeSynchroBonusOnTile,
  setCurrentSynchroPosition,
} from "../backend/synchro/Synchro";
import { SynchroBonus } from "../backend/synchro/SynchroBonus";
import { valuesOfCached } from "../utils/CoreUtils";
import { formatNumber } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import { SynchroGridSelectors } from "./synchro/SynchroGridSelectors";
import Divider from "./utility/Divider";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { GameIcon } from "./utility/icons/GameIcon";
import { useGameState } from "./utility/useGameState";

function SynchroBonusRow(props: { bonus: SynchroBonus; amount: number }) {
  const state = useGameState();
  const theme = useTheme();

  const visible = props.bonus.isVisible(state);
  const effect = props.bonus.getEffect(
    state,
    props.amount < 1 ? 1 : props.amount,
  );

  return useMemo(
    () => (
      <View
        style={[
          Styles.flexRowAlignCenter,
          props.amount == 0 && { opacity: 0.5 },
        ]}
      >
        <GameIcon
          icon={props.bonus.getElement1().toLowerCase()}
          style={Styles.mr4}
        />
        <GameIcon
          icon={props.bonus.getElement2().toLowerCase()}
          style={Styles.mr4}
        />
        <View style={Styles.ml4}>
          <Text style={{ fontWeight: "bold" }}>
            {t("x{{amount}}", { amount: formatNumber(props.amount) })}
          </Text>
        </View>
        <View style={[Styles.ml16, Styles.flex]}>
          <Markdown
            style={
              !props.bonus.isBeneficial() &&
              props.amount > 0 && { color: theme.colors.error }
            }
          >
            {visible ? effect : t("???")}
          </Markdown>
        </View>
      </View>
    ),
    [visible, effect, props.amount],
  );
}

function SynchroBottomComponent(props) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const activeBonuses = getSortedSynchroBonuses(state);

  const topPart = useMemo(
    () => (
      <View>
        <Divider />
        <View style={Styles.mv8}>
          <Text h3>{t("Bonuses")}</Text>
        </View>
        {activeBonuses.length == 0 && (
          <Text style={{ fontStyle: "italic" }}>{t("No bonuses yet!")}</Text>
        )}
      </View>
    ),
    [activeBonuses.length],
  );

  return (
    <View style={Styles.flex}>
      {topPart}
      <ScrollView style={Styles.flex}>
        {activeBonuses.map(({ bonus, amount }) => (
          <View key={bonus.getId()} style={Styles.pv4}>
            <SynchroBonusRow bonus={bonus} amount={amount} />
          </View>
        ))}
      </ScrollView>
    </View>
  );
}

function SynchroGridItem(props: {
  x: number;
  y: number;
  selected: boolean;
  error: boolean;
  onPress?: () => void;
}) {
  const state = useGameState();
  const theme = useTheme();

  const element = getSynchroPosition(state, props.x, props.y);

  const translationArgs = {
    x: formatNumber(props.x + 1),
    y: formatNumber(props.y + 1),
    element: element != null ? translateElement(element) : t("Empty"),
  };

  return useMemo(
    () => (
      <TouchableOpacity
        onPress={props.onPress}
        accessible={true}
        accessibilityLabel={
          props.selected
            ? t(
                "Position {{x}}, {{y}} - Selected - {{element}}",
                translationArgs,
              )
            : t("Position {{x}}, {{y}} - {{element}}", translationArgs)
        }
        accessibilityRole="button"
      >
        <View
          style={{
            width: 34,
            height: 34,
            borderWidth: 1,
            borderColor: props.error
              ? theme.colors.error
              : theme.colors.outline,
            backgroundColor:
              props.selected && props.error
                ? theme.colors.onErrorContainer
                : props.selected
                ? theme.colors.secondaryContainer
                : props.error
                ? theme.colors.errorContainer
                : undefined,
          }}
        >
          {element != null && <GameIcon icon={element.toLowerCase()} />}
        </View>
      </TouchableOpacity>
    ),
    [
      props.x,
      props.y,
      element,
      props.onPress,
      props.selected,
      props.error,
      theme,
    ],
  );
}

function SynchroGrid(props: {
  onGridPress: (x: number, y: number) => void;
  selectedPosition: { x: number; y: number } | undefined;
}) {
  const state = useGameState();
  const theme = useTheme();

  const rows = useMemo(() => {
    const rows = [];
    for (let i = 0; i < SYNCHRO_ROWS; i++) {
      const currentRow = [];
      for (let j = 0; j < SYNCHRO_COLS; j++) {
        currentRow.push(
          <SynchroGridItem
            x={j}
            y={i}
            selected={
              props.selectedPosition?.x == j && props.selectedPosition?.y == i
            }
            error={hasNegativeSynchroBonusOnTile(state, j, i)}
            key={i.toString() + j.toString()}
            onPress={props.onGridPress.bind(null, j, i)}
          />,
        );
      }
      rows.push(
        <View key={i} style={Styles.flexRowAlignCenter}>
          {currentRow}
        </View>,
      );
    }
    return rows;
  }, [
    JSON.stringify(getCurrentSynchroStrategy(state)),
    props.selectedPosition?.x,
    props.selectedPosition?.y,
  ]);

  return useMemo(
    () => (
      <View style={{ borderWidth: 1, borderColor: theme.colors.outline }}>
        {rows}
      </View>
    ),
    [theme, rows],
  );
}

function SynchroElement(props: {
  element: SpellElement | undefined;
  onPress: (element: SpellElement | undefined) => void;
}) {
  const state = useGameState();
  const theme = useTheme();

  const element = props.element;

  const uses = getTotalOccurrencesInSynchroGrid(state, element);
  const max = calculateMaxOccurrencesInSynchroGrid(state, element);

  return useMemo(() => {
    if (element == undefined) {
      return (
        <View style={[Styles.flexCenter, { alignSelf: "flex-start" }]}>
          <TouchableOpacity
            onPress={props.onPress.bind(null, element)}
            accessible={true}
            accessibilityLabel={t("Clear position")}
            accessibilityRole="button"
          >
            <View
              style={[
                styles.itemContainerStyle,
                { borderColor: theme.colors.outlineVariant },
              ]}
            >
              <GameIcon icon="x" />
            </View>
          </TouchableOpacity>
        </View>
      );
    }

    return (
      <View style={Styles.flexCenter}>
        <TouchableOpacity
          onPress={props.onPress.bind(null, element)}
          accessible={true}
          accessibilityLabel={t("Set to {{element}}", {
            element: translateElement(element),
          })}
          accessibilityRole="button"
        >
          <View
            style={[
              styles.itemContainerStyle,
              { borderColor: theme.colors.outlineVariant },
            ]}
          >
            <GameIcon icon={element.toLowerCase()} />
          </View>
        </TouchableOpacity>
        <View style={Styles.mt4}>
          <Text style={{ fontSize: 12 }}>
            {t("{{left}}/{{max}}", {
              left: formatNumber(max - uses),
              max: formatNumber(max),
            })}
          </Text>
        </View>
      </View>
    );
  }, [element, props.onPress, theme, max, uses]);
}

export default function Synchro(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const [selectedPosition, setSelectedPosition] = useState<{
    x: number;
    y: number;
  }>(null);
  const onGridPress = useCallback(
    (x: number, y: number) => setSelectedPosition({ x, y }),
    [setSelectedPosition],
  );

  useEffect(
    () => setSelectedPosition(undefined),
    [state.world.synchro.currentGrid],
  );

  const onElementPress = useCallback(
    (element: SpellElement) =>
      stateRef.current.apply((state) => {
        return setCurrentSynchroPosition(
          state,
          selectedPosition.x,
          selectedPosition.y,
          element,
        );
      }),
    [selectedPosition],
  );

  const synchroGrid = useMemo(
    () => (
      <SynchroGrid
        onGridPress={onGridPress}
        selectedPosition={selectedPosition}
      />
    ),
    [onGridPress, selectedPosition],
  );

  const synchroElementButtons = useMemo(() => {
    const buttons = getFullyUnlockedElements(state)
      .sort(
        (a, b) =>
          valuesOfCached(SpellElement).indexOf(a) -
          valuesOfCached(SpellElement).indexOf(b),
      )
      .map((element) => (
        <SynchroElement
          key={element}
          onPress={onElementPress}
          element={element}
        />
      ));
    buttons.push(
      <SynchroElement
        key="clear"
        onPress={onElementPress}
        element={undefined}
      />,
    );
    return buttons;
  }, [JSON.stringify(getFullyUnlockedElements(state)), onElementPress]);

  const showIntroMessage = !hasWorldFlag(state, Flag.EverUsedSynchro);

  return useMemo(
    () => (
      <View style={[Styles.p4, Styles.flex]}>
        <Text h1>{t("Synchro")}</Text>
        <View style={[Styles.flexCenter, Styles.mt8]}>{synchroGrid}</View>
        <View style={[Styles.flexCenter, Styles.mt8, { minHeight: 120 }]}>
          <View
            style={[
              Styles.flexRowAlignCenter,
              {
                width: 34 * 7 + 8 * 6,
                gap: 8,
                flexWrap: "wrap",
                justifyContent: "center",
              },
            ]}
          >
            {selectedPosition != null && synchroElementButtons}
            {selectedPosition == null && showIntroMessage && (
              <Text style={{ fontStyle: "italic" }}>
                {t(
                  "Welcome to the Synchro screen! You can get started with your Synchro Board by pressing any of the tiles in the grid above.",
                )}
              </Text>
            )}
          </View>
        </View>
        <View style={Styles.mt8}>
          <SynchroGridSelectors />
        </View>
        <View style={[Styles.flex, Styles.mt8]}>
          <SynchroBottomComponent />
        </View>
      </View>
    ),
    [selectedPosition, synchroElementButtons, synchroGrid, showIntroMessage],
  );
}

const styles = StyleSheet.create({
  itemContainerStyle: {
    width: 34,
    height: 34,
    borderWidth: 1,
  },
});
