import { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { loadAutocastStrategy } from "../../backend/autocast/Autocast";
import { keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { Button } from "./Button";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

export function AutocastLoadoutLoadContainer(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const loadoutObjects = state.global.autocast.savedStrategies ?? [];
  const loadPosition = useCallback((position: number) => {
    stateRef.current.apply((state) => loadAutocastStrategy(state, position));
  }, []);
  return useMemo(
    () => (
      <View style={styles.loadoutsLoadRow}>
        <View style={styles.loadTextContainer}>
          <Text>{t("Load:")}</Text>
        </View>
        {keysOf(loadoutObjects).length > 0 ? (
          keysOf(loadoutObjects).map((key) => {
            const loadoutObject = loadoutObjects[key];
            if (loadoutObject == null) {
              return null;
            }
            const name =
              loadoutObject?.name != "" && loadoutObject?.name != null
                ? loadoutObject?.name
                : t("Slot {{slot}}", {
                    slot:
                      (typeof key === "string" ? Number.parseInt(key) : key) +
                      1,
                  });
            return (
              <Button
                key={key}
                style={styles.loadButton}
                labelStyle={styles.smallButtonTitle}
                compact={true}
                onPress={loadPosition.bind(null, key)}
              >
                {name}
              </Button>
            );
          })
        ) : (
          <Text style={styles.noLoadoutsText}>
            {t("No loadouts have been saved yet!")}
          </Text>
        )}
      </View>
    ),
    [JSON.stringify(loadoutObjects), loadPosition],
  );
}

const styles = StyleSheet.create({
  loadoutsLoadRow: {
    flexDirection: "row",
    flexWrap: "wrap",
    alignItems: "center",
    gap: 8,
  },
  loadButton: {},
  loadTextContainer: {
    marginRight: 8,
  },
  noLoadoutsText: {
    fontStyle: "italic",
  },
  smallButtonTitle: {
    fontSize: 12,
  },
});
