import { t } from "../../utils/I18n";
import { hasFlag, hasWorldFlag, setFlag, setWorldFlag } from "../Flags";
import { GameState } from "../GameState";
import {
  SpellElement,
  getElementLevel,
  getUnlockedElements,
} from "../spells/Elements";

export enum StorylineStatus {
  HIDDEN = "HIDDEN",
  LOCKED = "LOCKED",
  UNLOCKED = "UNLOCKED",
  HINT_REVEALED = "HINT_REVEALED",
  COMPLETED = "COMPLETED",
  BONUS_ACTIVE = "BONUS_ACTIVE",
}

export const STORYLINE_STATUS_ORDER = [
  StorylineStatus.LOCKED,
  StorylineStatus.UNLOCKED,
  StorylineStatus.HINT_REVEALED,
  StorylineStatus.COMPLETED,
  StorylineStatus.BONUS_ACTIVE,
];

export function translateStorylineStatus(status: StorylineStatus): string {
  switch (status) {
    case StorylineStatus.HIDDEN:
      return t("Hidden");
    case StorylineStatus.LOCKED:
      return t("Undiscovered");
    case StorylineStatus.UNLOCKED:
      return t("Discoverable");
    case StorylineStatus.HINT_REVEALED:
      return t("Hint Available");
    case StorylineStatus.COMPLETED:
      return t("Completed");
    case StorylineStatus.BONUS_ACTIVE:
      return t("Bonus Active");
  }
}

export class Storyline {
  id: string;
  name: string;
  // Order semantics
  // Increment by 10 per storyline "group" and by 1 per outcome on that
  // storyline.
  order: number;
  mindRequirementForHint: number;
  hintDescription: (state: GameState) => string;
  getBonusDescription: (state: GameState) => string;
  isHintUnlockable: (state: GameState) => boolean;
  isHidden?: (state: GameState) => boolean;

  constructor(
    id: string,
    name: string,
    order: number,
    mindRequirementForHint: number,
    hintDescription: string | ((state: GameState) => string),
    getBonusDescription: string | ((state: GameState) => string),
    isHintUnlockable: (state: GameState) => boolean,
    isHidden?: (state: GameState) => boolean,
  ) {
    this.id = id;
    this.name = name;
    this.order = order;
    this.mindRequirementForHint = mindRequirementForHint;
    this.hintDescription =
      typeof hintDescription === "string"
        ? (state) => hintDescription
        : hintDescription;
    this.getBonusDescription =
      typeof getBonusDescription === "string"
        ? (state) => getBonusDescription
        : getBonusDescription;
    this.isHintUnlockable = isHintUnlockable;
    this.isHidden = isHidden ?? ((_state) => false);
  }

  getId(): string {
    return this.id;
  }

  getName(): string {
    return this.name;
  }

  getCompletedFlag(): string {
    return "storyline_" + this.getId() + "_completed";
  }

  isCompleted(state: GameState): boolean {
    return hasWorldFlag(state, "storyline_" + this.getId() + "_completed");
  }

  isCompletedThisRun(state: GameState): boolean {
    return hasFlag(state, "storyline_" + this.getId() + "_completed");
  }

  isBonusActive(state: GameState): boolean {
    return hasWorldFlag(state, "storyline_" + this.getId() + "_bonus_active");
  }

  isHintRevealed(state: GameState): boolean {
    return hasWorldFlag(state, "storyline_" + this.getId() + "_hint_revealed");
  }

  complete(state: GameState): GameState {
    state = setFlag("storyline_" + this.getId() + "_completed")(state);
    return setWorldFlag("storyline_" + this.getId() + "_completed")(state);
  }

  activateBonus(state: GameState): GameState {
    state = setFlag("storyline_" + this.getId() + "_bonus_active")(state);
    return setWorldFlag("storyline_" + this.getId() + "_bonus_active")(state);
  }

  revealHint(state: GameState): GameState {
    state = setFlag("storyline_" + this.getId() + "_hint_revealed")(state);
    return setWorldFlag("storyline_" + this.getId() + "_hint_revealed")(state);
  }

  shouldRevealHint(state: GameState): boolean {
    return (
      !this.isHintRevealed(state) &&
      this.isHintUnlockable(state) &&
      hasWorldFlag(state, "storyline_diviningTheWorld_bonus_active") &&
      getUnlockedElements(state).includes(SpellElement.Mind) &&
      getElementLevel(state, SpellElement.Mind) >= this.mindRequirementForHint
    );
  }

  getStatus(state: GameState): StorylineStatus {
    if (this.isHidden(state)) {
      return StorylineStatus.HIDDEN;
    }
    if (this.isBonusActive(state)) {
      return StorylineStatus.BONUS_ACTIVE;
    }
    if (this.isCompleted(state)) {
      return StorylineStatus.COMPLETED;
    }
    if (this.isHintRevealed(state)) {
      return StorylineStatus.HINT_REVEALED;
    }
    if (this.isHintUnlockable(state)) {
      return StorylineStatus.UNLOCKED;
    }
    return StorylineStatus.LOCKED;
  }
}
