import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ActionEffect } from "../action/Action";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationValueType } from "../transformation/TransformationTags";
import { CombatClass } from "./CombatClasses";

class Battlemage extends CombatClass {
  getId(): string {
    return "battlemage";
  }
  getDisplayName(): string {
    return t("Battlemage");
  }
  protected getBaseClassEffects(): Record<string, ActionEffect> {
    return {
      attackAndDefense: {
        value: 0.1,
        valueType: TransformationValueType.Percent,
      },
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Particularly strong physically, a Battlemage has undergone special training to make their body adept at combat, much like a knight - but while still being able to use potent magic.",
    );
  }
  getDisplayEffect(state: GameState): string {
    const effects = this.getClassEffects(state);
    const explanations = this.getClassEffectExplanations(state);
    return t(
      "^{{attackMultiplier}}^<{{attackExplanation}}>:attack:, ^{{attackMultiplier}}^<{{attackExplanation}}>:defense:",
      {
        attackMultiplier: formatMultiplier(1.0 + effects.attackAndDefense),
        attackExplanation: explanations.attackAndDefense,
      },
    );
  }
}

export const battlemage = new Battlemage();

registerTransformation(
  [
    [AttackTarget.Player, CombatStat.Attack],
    [AttackTarget.Player, CombatStat.Defense],
  ],
  battlemage.getId(),
  battlemage.getDisplayName(),
  TransformationType.Multiplier,
  (state) =>
    battlemage.isActive(state)
      ? 1.0 + battlemage.getClassEffects(state).attackAndDefense
      : 1.0,
);
