import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";

class EfficientExploration extends Enhancement {
  getId(): string {
    return "efficientExploration";
  }
  getBaseName(): string {
    return t("Efficient Exploration");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_EXPLORATION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 200,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 1,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t("Refine your scanning techniques in order to explore faster.");
  }
  getDisplayEffect(state: GameState): string {
    return t("-25% Exploration delay");
  }
}

export const efficientExploration = new EfficientExploration();

registerTransformation(
  [[TransformationTags.ExplorationDelay]],
  efficientExploration.getId(),
  efficientExploration.getBaseName(),
  TransformationType.Multiplier,
  (state) => (efficientExploration.isPurchased(state) ? 0.75 : 1.0),
);
