import { t } from "i18next";
import { valuesOf } from "../../utils/CoreUtils";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import { clearAutocastCaches } from "../autocast/Autocast";
import {
  addToInventory,
  removeFromInventoryWithAnyParams,
} from "../items/Inventory";
import { getAllKnownItemCompendiumInfos } from "../items/ItemCompendium";
import { getTransmutationSpellForItem } from "../items/transmute/TransmutationSpell";
import { getPrimaryElements } from "../spells/Elements";
import type { Spell } from "../spells/Spell";
import { Spells, clearAllSpellCaches } from "../spells/Spells";
import {
  TransformationType,
  registerTransformation,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Spellcraft, generateSpellcraftMultiplier } from "./Spellcraft";
import { SpellcraftData } from "./SpellcraftData";
import { Spellcrafts } from "./Spellcrafts";

export const MAX_VATS_PER_SPELL = 2;

export function getSpellcraftVatsForSpell(
  state: GameState,
  spellId: string,
): SpellcraftData[] {
  return valuesOf(state.run.spellcrafts).filter(
    (data) => data.spellId == spellId,
  );
}

export function canLoadMultipleVats(
  state: GameState,
  spellcraft: Spellcraft,
): boolean {
  const item = spellcraft.getBaseItem();
  const transmutation = getTransmutationSpellForItem(item);
  if (transmutation == null) {
    return false;
  }

  const element = transmutation.getElement();
  return getPrimaryElements(state).includes(element);
}

export function getVisibleSpellcrafts(state: GameState): Spellcraft[] {
  return Spellcrafts.getAll().filter((spellcraft) => {
    const item = spellcraft.getBaseItem();
    return getAllKnownItemCompendiumInfos(state).some(
      (info) => info.item == item,
    );
  });
}

export function getVisibleSpellcraftsForSpell(
  state: GameState,
  spell: Spell,
): Spellcraft[] {
  const spellcrafts = getVisibleSpellcrafts(state);
  return spellcrafts.filter((spellcraft) =>
    spellcraft.isSpellCompatible(spell),
  );
}

export function canApplySpellcraftToSpell(
  state: GameState,
  spellId: string,
  spellcraft: Spellcraft,
): boolean {
  const allVats = valuesOf(state.run.spellcrafts);
  const vats = getSpellcraftVatsForSpell(state, spellId).filter(
    (vat) => vat.spellcraftId != null,
  );

  if (vats.length >= MAX_VATS_PER_SPELL) {
    return false;
  }

  if (!canLoadMultipleVats(state, spellcraft)) {
    // Check if any other vat is loaded with this spellcraft
    for (let vat of allVats) {
      if (vat.spellcraftId == spellcraft.getId()) {
        return false;
      }
    }
  }

  // Check if the spell's vats already have this spellcraft
  for (let vat of vats) {
    if (vat.spellcraftId == spellcraft.getId()) {
      return false;
    }
  }

  return true;
}

export function assignSpellcraftToVat(
  state: GameState,
  vatId: string,
  spellcraftId: string,
): GameState {
  const spellcraft = Spellcrafts.getById(spellcraftId);
  const vat = state.run.spellcrafts[vatId];
  if (vat.spellcraftId == spellcraftId) {
    return state;
  }

  if (vat.spellId == null) {
    // Do not assign
    return state;
  }
  const spell = Spells.getById(vat.spellId);
  if (!spellcraft.isSpellCompatible(spell)) {
    // Do not assign
    return state;
  }

  // Check if it can be applied
  if (!canApplySpellcraftToSpell(state, vat.spellId, spellcraft)) {
    return state;
  }

  // Check the cost
  if (!spellcraft.canAfford(state)) {
    return state;
  }

  if (vat.spellcraftId != null) {
    // First, unassign the current one to recover the mats
    state = removeSpellcraftFromVat(state, vatId);
  }

  // Pay the cost
  state = removeFromInventoryWithAnyParams(
    spellcraft.getBaseItem(),
    1000,
    state,
  );

  // Assign
  vat.spellcraftId = spellcraftId;
  state = clearAllSpellCaches(state);
  state = clearAutocastCaches(state);
  return state;
}

export function removeSpellcraftFromVat(
  state: GameState,
  vatId: string,
): GameState {
  const vat = state.run.spellcrafts[vatId];
  if (vat.spellId == null) {
    // Do not do anything
    return state;
  }

  // Refund the cost
  const spellcraft = Spellcrafts.getByIdNullable(vat.spellcraftId);
  if (spellcraft != null) {
    state = addToInventory(
      {
        itemId: spellcraft.getBaseItem().getId(),
        params: spellcraft.getBaseItem().getDefaultParams(),
      },
      1000,
      state,
    );
  }

  // Remove
  vat.spellcraftId = undefined;
  state = clearAllSpellCaches(state);
  state = clearAutocastCaches(state);
  return state;
}

export function assignSpellToVat(
  state: GameState,
  vatId: string,
  spellId: string,
): GameState {
  if (state.run.spellcrafts[vatId].spellcraftId != null) {
    const spellcraft = Spellcrafts.getById(
      state.run.spellcrafts[vatId].spellcraftId,
    );
    if (
      !spellcraft.isSpellCompatible(Spells.getById(spellId)) ||
      !canApplySpellcraftToSpell(state, spellId, spellcraft)
    ) {
      state = removeSpellcraftFromVat(state, vatId);
    }
  }
  state.run.spellcrafts[vatId].spellId = spellId;
  state = clearAllSpellCaches(state);
  state = clearAutocastCaches(state);
  return state;
}

export function createNewVat(state: GameState): GameState {
  const newId = Math.random().toString();
  state.run.spellcrafts[newId] = {
    spellcraftId: undefined,
    spellId: undefined,
    vatId: newId,
  };
  return state;
}

export function destroyVat(state: GameState, vatId: string): GameState {
  if (state.run.spellcrafts[vatId] == null) {
    return state;
  }

  if (state.run.spellcrafts[vatId].spellcraftId != null) {
    state = removeSpellcraftFromVat(state, vatId);
  }

  delete state.run.spellcrafts[vatId];
  return state;
}

export function getSpellcraftVats(state: GameState): SpellcraftData[] {
  return valuesOf(state.run.spellcrafts);
}

export function isSpellcraftUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.SpellcraftUnlocked);
}

export function isSpellcraftEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.SpellcraftUnlocked);
}

function spellcraftTransform(
  effectId: string,
  state: GameState,
  params: Record<string, any>,
) {
  const spellId = params?.action?.getId?.();
  if (spellId == null) {
    return 1.0;
  }

  const spellcrafts = getSpellcraftVatsForSpell(state, spellId);
  return spellcrafts.reduce((prev, current) => {
    const spellcraft = Spellcrafts.getByIdNullable(current.spellcraftId);
    return (
      prev *
      generateSpellcraftMultiplier(
        spellcraft?.getBaseMultipliers?.()?.[effectId] ?? 0,
        spellcraft?.getBonusEffect?.(state, effectId) ?? 0,
      )
    );
  }, 1.0);
}

export function loadGenericSpellcraftTransformations() {
  registerTransformation(
    [[TransformationTags.Spell, TransformationTags.AutocastPointsConsumption]],
    "spellcraftWizardPower",
    t("Spellcraft"),
    TransformationType.Multiplier,
    spellcraftTransform.bind(null, "wizardPower"),
  );

  registerTransformation(
    [
      [
        TransformationTags.Spell,
        TransformationTags.ActionEffect,
        TransformationTags.ActionMagnitude,
      ],
      [
        TransformationTags.Spell,
        TransformationTags.ActionEffect,
        TransformationTags.TemporaryEffectMagnitude,
      ],
      [
        TransformationTags.Spell,
        TransformationTags.ActionEffect,
        TransformationTags.ChannelingEfficiency,
      ],
    ],
    "spellcraftPower",
    t("Spellcraft"),
    TransformationType.Multiplier,
    spellcraftTransform.bind(null, "power"),
  );

  registerTransformation(
    [[TransformationTags.Spell, TransformationTags.SpellCooldown]],
    "spellcraftCooldown",
    t("Spellcraft"),
    TransformationType.Multiplier,
    spellcraftTransform.bind(null, "cooldown"),
  );

  registerTransformation(
    [
      [
        TransformationTags.Spell,
        TransformationTags.ActionEffect,
        TransformationTags.TemporaryEffectDuration,
      ],
    ],
    "spellcraftTemporaryEffectDuration",
    t("Spellcraft"),
    TransformationType.Multiplier,
    spellcraftTransform.bind(null, "temporaryEffectDuration"),
  );

  registerTransformation(
    [[TransformationTags.Spell, TransformationTags.Cost, Resource.Mana]],
    "spellcraftManaCost",
    t("Spellcraft"),
    TransformationType.Multiplier,
    spellcraftTransform.bind(null, "manaCost"),
  );
}
