import { GameState } from "../GameState";
import { applyTransformations } from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { registerTimeTickListener } from "./TimeTick";

export function loadTotalTimePlayed() {
  const gameSpeedTags = [TransformationTags.GameSpeed];
  registerTimeTickListener(
    "TotalTimePlayed",
    (state, timeElapsedSec, isWarp) => {
      state.global.secondsPlayed += timeElapsedSec;
      state.world.secondsPlayed += timeElapsedSec;
      state.run.secondsPlayed += timeElapsedSec;
      if (!isWarp) {
        // To calculate active play time we need to de-transform the timeElapsedSec
        const speed = applyTransformations(gameSpeedTags, state, 1.0);
        if (speed > 0) {
          state.global.activeSecondsPlayed += timeElapsedSec / speed;
          state.world.activeSecondsPlayed += timeElapsedSec / speed;
          state.run.activeSecondsPlayed += timeElapsedSec / speed;
        }
      }
      return state;
    },
  );
}

export function getTotalSecondsPlayed(state: GameState) {
  return state.global.secondsPlayed || 0;
}

export function getTotalActiveSecondsPlayed(state: GameState) {
  return state.global.activeSecondsPlayed || 0;
}

export function getSecondsPlayed(state: GameState) {
  return state.run.secondsPlayed || 0;
}
