import { MaterialIcons } from "@expo/vector-icons";
import React, { useMemo } from "react";
import { Image, StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import { AttackTarget } from "../../backend/exploration/AttackTarget";
import {
  TemporaryEffects,
  getTemporaryEffectsForTarget,
} from "../../backend/temporaryeffects/TemporaryEffects";
import { getSecondsPlayed } from "../../backend/timetick/TotalTimePlayed";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import Styles from "./Styles";
import { Text } from "./Text";
import Tooltip from "./tooltip/PaperTooltip";
import { useGameState } from "./useGameState";

const HITSLOP = { top: 8, bottom: 8, left: 8, right: 8 };

function TemporaryEffectRow(props: {
  tooltip: string;
  text: string;
  icon: any;
}) {
  const theme = useTheme();
  const tooltip = useMemo(
    () => (
      <View style={Styles.mr8}>
        <Tooltip title={props.tooltip} hitSlop={HITSLOP}>
          <MaterialIcons
            name="info"
            size={16}
            color={theme.colors.onBackground}
          />
        </Tooltip>
      </View>
    ),
    [props.tooltip, theme],
  );
  const viewStyle = useMemo(
    () => [
      styles.row,
      {
        width: "100%",
      },
    ],
    [],
  );
  const image = useMemo(
    () => <Image source={props.icon} style={styles.icon} />,
    [props.icon],
  );
  return (
    <View style={viewStyle}>
      {tooltip}
      {image}
      <Text doNotScaleFonts={true} style={styles.boostText}>
        {props.text}
      </Text>
    </View>
  );
}

const TemporaryEffectRowMemo = React.memo(TemporaryEffectRow);

export function TemporaryEffectsViewer(props: {
  target?: AttackTarget;
  isVisible?: boolean;
  accessibilityHeader?: string;
  maxElements?: number;
  skipPermacast?: boolean;
}) {
  const state = useGameState();
  const target = props.target ?? AttackTarget.Player;
  const temporaryEffects = getTemporaryEffectsForTarget(state, target)
    .concat(
      target == AttackTarget.Player
        ? getTemporaryEffectsForTarget(state, AttackTarget.Familiar)
        : [],
    )
    .sort(
      (a, b) =>
        (target == AttackTarget.Enemy ? -1 : 1) *
          ((TemporaryEffects.getById(a.id).isBeneficial() ? 10000 : 0) -
            (TemporaryEffects.getById(b.id).isBeneficial() ? 10000 : 0)) +
        (a.fromPermacast ? 1000 : 0) -
        (b.fromPermacast ? 1000 : 0) +
        (a.fromPouch ? 100 : 0) -
        (b.fromPouch ? 100 : 0) +
        (a.fromAutocast ? 10 : 0) -
        (b.fromAutocast ? 10 : 0),
    );
  const viewStyle = useMemo(
    () => [
      styles.row,
      {
        width: "100%",
      },
    ],
    [],
  );
  return useMemo(() => {
    const rows = temporaryEffects
      .slice(0, props.maxElements)
      .map((temporaryEffect, idx) => {
        const { endTime } = temporaryEffect;
        const temporaryEffectObject = TemporaryEffects.getById(
          temporaryEffect.id,
        );
        const name = temporaryEffectObject.getDisplayName();
        const timeLeftSec = endTime - getSecondsPlayed(state);
        const isPermacast = temporaryEffect.fromPermacast;
        const text =
          isPermacast && !props.skipPermacast
            ? t("{{familiarPrefix}}{{temporaryEffectName}} (P)", {
                temporaryEffectName: name,
                familiarPrefix:
                  temporaryEffect.target == AttackTarget.Familiar
                    ? t("(Familiar) ")
                    : "",
              })
            : t(
                "{{familiarPrefix}}{{temporaryEffectName}} ({{temporaryEffectDuration}} sec)",
                {
                  temporaryEffectName: name,
                  temporaryEffectDuration: formatNumber(timeLeftSec),
                  familiarPrefix:
                    temporaryEffect.target == AttackTarget.Familiar
                      ? t("(Familiar) ")
                      : "",
                },
              );
        return (
          <View
            key={temporaryEffect.id + temporaryEffect.target}
            style={idx > 0 && Styles.mt2}
          >
            <TemporaryEffectRowMemo
              text={text}
              tooltip={temporaryEffectObject.getDisplayDescription(
                state,
                temporaryEffect,
              )}
              icon={temporaryEffectObject.getIcon()}
            />
          </View>
        );
      });
    let andMoreComponent = null;
    if (rows.length < temporaryEffects.length) {
      andMoreComponent = (
        <View key="andMore" style={viewStyle}>
          <Text doNotScaleFonts={true} style={styles.boostText}>
            {t("And {{notShownElements}} more...", {
              notShownElements: formatNumber(
                temporaryEffects.length - rows.length,
              ),
            })}
          </Text>
        </View>
      );
    }
    return (
      <View style={styles.container}>
        {temporaryEffects.length > 0 && props.accessibilityHeader != null && (
          <View
            style={styles.accessibilityHidden}
            accessible={true}
            accessibilityLabel={props.accessibilityHeader}
            accessibilityRole="header"
            accessibilityElementsHidden={false}
          />
        )}
        {rows}
        {andMoreComponent}
      </View>
    );
  }, [
    props.isVisible !== false ? state : false,
    props.isVisible !== false ? temporaryEffects : false,
    props.maxElements,
  ]);
}

const styles = StyleSheet.create({
  container: {
    justifyContent: "center",
    alignItems: "stretch",
  },
  icon: {
    height: 16,
    width: 16,
  },
  row: {
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "center",
    marginTop: 4,
  },
  boostText: {
    flex: 1,
    marginLeft: 8,
  },
  accessibilityHidden: {
    width: 1,
    height: 1,
    position: "absolute",
    left: 0,
    top: 0,
  },
});
