import { GameState } from "./GameState";

export function getUICommands(state: GameState) {
  return state.run.uiCommands || [];
}

export function clearUICommands(state: GameState) {
  state.run.uiCommands = [];
  return state;
}

export function addUICommand(state: GameState, command: string): GameState {
  state.run.uiCommands.push(command);
  return state;
}
