import memoize from "sonic-memoize";
import { keysOf, valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { cacheByArbitraryVersion, cacheByTickCount } from "../Caching";
import { hasWorldFlag } from "../Flags";
import { GameState, createHypotheticalState } from "../GameState";
import { Resource, getResourceCap } from "../Resources";
import { DoActionArgs } from "../action/Action";
import { ActionArea, ActionSubcategory } from "../action/ActionAreas";
import { SpellAutocastCategory } from "../autocast/Autocast";
import { Identifiable } from "../generic/Identifiable";
import { Spell } from "../spells/Spell";
import { Spells } from "../spells/Spells";
import {
  applyTransformationsCached,
  applyTransformationsPermaCached,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import {
  BuildingAmountListeners,
  BuildingData,
  getBuildingAmount,
  getBuildingAmountTurnedOn,
  getFreeLand,
} from "./Buildings";

export abstract class Building implements Identifiable {
  abstract getId(): string;
  abstract getName(): string;
  abstract getDisplayDescription(state: GameState): string;
  abstract getDisplayEffect(state: GameState): string;
  abstract canTurnOff(): boolean;
  abstract getBaseLandRequired(): number;
  landTags = [this.getId(), TransformationTags.LandCost];
  getLandRequired(state: GameState): number {
    const baseLandRequired = this.getBaseLandRequired();
    const postTransform = applyTransformationsPermaCached(
      this.landTags,
      state,
      baseLandRequired,
    );
    return postTransform;
  }
}

export abstract class BuildingSpell extends Spell {
  abstract getBaseResourceCost(): Partial<Record<Resource, number>>;
  abstract getBaseResourceScale(): Partial<Record<Resource, number>>;

  constructor() {
    super();
  }

  abstract getBuilding(): Building;

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.OTHER;
  }

  canAutoPurchaseMax(): boolean {
    return true;
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    // Must populate by subclasses
    return {};
  }

  getSpellName(): string {
    return t("Build {{buildingName}}", {
      buildingName: this.getBuilding().getName(),
    });
  }

  getDisplayName(state: GameState): string {
    const amount = getBuildingAmount(state, this.getBuilding());
    const buildingName = this.getBuilding().getName();
    if (amount == 0) {
      return buildingName;
    }

    if (this.canTurnOff()) {
      const buildingAmountTurnedOn = getBuildingAmountTurnedOn(
        state,
        this.getBuilding(),
      );
      return t(
        "{{buildingName}} ({{buildingAmountTurnedOn}}/{{buildingAmount}})",
        {
          buildingName,
          buildingAmount: amount,
          buildingAmountTurnedOn,
        },
      );
    }
    return t("{{buildingName}} ({{buildingAmount}})", {
      buildingName,
      buildingAmount: getBuildingAmount(state, this.getBuilding()),
    });
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return cacheByTickCount(state, this.getId() + "cost", (state) => ({
      resources: this.getResourceCost(state),
      items: {},
    }));
  }

  getTags(): string[] {
    return ([TransformationTags.Building] as string[]).concat(super.getTags());
  }

  costTags = memoize((key) => [
    ...this.getTags(),
    TransformationTags.Cost,
    key,
  ]);
  costScaleTags = memoize((key) => [
    ...this.getTags(),
    TransformationTags.CostScale,
    key,
  ]);

  getResourceCost(state: GameState): Record<Resource, number> {
    const currentAmount = getBuildingAmount(state, this.getBuilding());
    const baseCost = this.getBaseResourceCost();
    const scale = this.getBaseResourceScale();
    const finalCost: Record<string, number> = {};
    Object.keys(baseCost).forEach((key) => {
      const baseCostForKey = applyTransformationsCached(
        this.costTags(key),
        state,
        baseCost[key],
      );
      const costScaleForKey = applyTransformationsCached(
        this.costScaleTags(key),
        state,
        scale[key] - 1,
      );

      finalCost[key] = Math.ceil(
        baseCostForKey * Math.pow(1 + costScaleForKey, currentAmount),
      );
    });
    return finalCost;
  }

  canTurnOff(): boolean {
    return this.getBuilding().canTurnOff();
  }

  getDisplayDescription(state: GameState): string {
    return this.getBuilding().getDisplayDescription(state);
  }

  getDisplayEffect(state: GameState): string {
    return this.getBuilding().getDisplayEffect(state);
  }

  getId(): string {
    return "build-" + this.getBuilding().getId();
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    throw new Error("Not used in building spells");
  }

  getBaseManaCost(state: GameState): number {
    throw new Error("Not used in building spells");
  }

  canAfford(state: GameState): boolean {
    const canAffordLand =
      getFreeLand(state) >= this.getBuilding().getLandRequired(state);
    if (!canAffordLand) {
      return false;
    }
    return super.canAfford(state);
  }

  isCapEnough(state: GameState): boolean {
    const landRequirement = this.getBuilding().getLandRequired(state);
    const freeLand = getFreeLand(state);

    return super.isCapEnough(state) && landRequirement <= freeLand;
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }

    const id = this.getBuilding().getId();
    state = this.payCost(state);
    if (state.run.buildings?.[id] == null) {
      state.run.buildings[id] = {
        current: 0,
        turnedOn: 0,
      };
    }
    const buildingData = state.run.buildings[id] as BuildingData;
    buildingData.current += 1;
    buildingData.turnedOn =
      getBuildingAmountTurnedOn(state, this.getBuilding()) + 1;
    //((args.isAutomatic && this.canTurnOff()) ? 0 : 1);
    if (buildingData.current > (state.world.maxFurnitureNumbers?.[id] ?? 0)) {
      state.world.maxFurnitureNumbers[id] = buildingData.current;
    }
    BuildingAmountListeners.getAll().forEach(
      (transform) => (state = transform(state, this.getBuilding())),
    );
    return state;
  }

  shouldNeverHaveDemolishBlockers(): boolean {
    return true;
  }

  // If true, this building will never cause demolish blockers.
  // Good for buildings like the Storage Expanders, which when demolished,
  // can lower
  shouldNotCountForDemolishBlockers(): boolean {
    return false;
  }

  getDemolishBlockers(state: GameState, amount: number): Building[] {
    if (this.shouldNeverHaveDemolishBlockers()) {
      return [];
    }
    return cacheByArbitraryVersion(
      state,
      this.getId() + "demolishBlockers",
      (s) => {
        let hypotheticalState = createHypotheticalState(s);

        let blockerBuildings = [];
        hypotheticalState = this._demolishUnsafe(hypotheticalState, amount);
        // Remove 1 of every other building
        for (let building of Spells.getAll().filter(
          (spell) => spell instanceof BuildingSpell && spell != this,
        ) as BuildingSpell[]) {
          if (
            getBuildingAmount(hypotheticalState, building.getBuilding()) > 0 &&
            !building.shouldNotCountForDemolishBlockers()
          ) {
            hypotheticalState = building._demolishUnsafe(hypotheticalState, 1);
            const resourceCost = building.getCost(hypotheticalState).resources;
            for (let resource of keysOf(resourceCost)) {
              if (
                resourceCost[resource] >
                  getResourceCap(hypotheticalState, resource) &&
                resourceCost[resource] <= getResourceCap(state, resource)
              ) {
                blockerBuildings.push(building.getBuilding());
                break;
              }
            }
            hypotheticalState.run.buildings[
              building.getBuilding().getId()
            ].current += 1;
          }
        }

        return blockerBuildings;
      },
      // This versioning function is not 100% correct, but it'll do in most cases
      (s) =>
        valuesOf(state.run.buildings)
          .map((buildingData) => buildingData.current)
          .reduce((prev, current, index) => prev + current * (index + 1), 0),
    );
  }

  shouldHideDemolish(state: GameState): boolean {
    return hasWorldFlag(state, "storyline_blessedLand_bonus_active");
  }

  canDemolish(state: GameState, amount: number): boolean {
    return this.getDemolishBlockers(state, amount).length == 0;
  }

  _demolishUnsafe(state: GameState, amount: number): GameState {
    const amountToDemolish = Math.min(
      getBuildingAmount(state, this.getBuilding()),
      amount,
    );
    const id = this.getBuilding().getId();
    if (state.run.buildings?.[id] == null) {
      state.run.buildings[id] = {
        current: 0,
        turnedOn: 0,
      };
    }
    const buildingData = state.run.buildings[id] as BuildingData;
    buildingData.current -= amountToDemolish;
    buildingData.turnedOn -= Math.min(buildingData.turnedOn, amountToDemolish);
    state.run.autoPurchaseActions[this.getId()] = false;
    BuildingAmountListeners.getAll().forEach(
      (transform) => (state = transform(state, this.getBuilding())),
    );
    return state;
  }

  demolish(state: GameState, amount: number): GameState {
    if (!this.canDemolish(state, amount)) {
      return state;
    }
    return this._demolishUnsafe(state, amount);
  }

  protected shouldIgnoreCanCastSpell(): boolean {
    return true;
  }
}
