import { t } from "../../../../utils/I18n";
import { Achievement } from "../../../Achievement";
import { registerAchievementUnlockerFromEnemy } from "../../../Achievements";
import { jumpEventToMessageIfExists } from "../../../events/Events";
import { Flag } from "../../../Flag";
import { setFlag } from "../../../Flags";
import { GameState } from "../../../GameState";
import { SpellElement } from "../../../spells/Elements";
import { Chilled } from "../../../temporaryeffects/Chilled";
import {
  getTemporaryEffectEndTime,
  grantTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { Wet } from "../../../temporaryeffects/Wet";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import {
  getCurrentEnemyHPFraction,
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { pushCombatActionResult } from "../../ExplorationActionResult";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class HeadInstructor extends Enemy {
  getId(): string {
    return "headInstructor";
  }

  getName(): string {
    return t("Head Instructor Giselle");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 1800,
      attack: 30,
      defense: 25,
      attackDelay: 2.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./headInstructor.png");
  }

  getLevel(): number {
    return 10;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 150;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 50;
  }

  getNextAction2(state: GameState): BattlerAction {
    if (Math.random() < 0.2) {
      return {
        name: t("Fireball"),
        delay: this.getAttackDelay(state) * 1.8,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 2.1,
            {
              tags: [SpellElement.Fire],
            },
          );
          return state;
        },
      };
    }

    if (Math.random() < 0.5) {
      return {
        name: t("Air Blast"),
        delay: this.getAttackDelay(state) * 0.35,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 0.5,
            {
              tags: [SpellElement.Air],
            },
          );
          return state;
        },
      };
    }

    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  getNextAction(state: GameState): BattlerAction {
    // Strategy:
    // First half is similar to the Animated Snowman
    // Second half is similar to the Blazing Turtle
    if (getCurrentEnemyHPFraction(state) < 0.5) {
      return this.getNextAction2(state);
    }

    if (
      Math.random() < 0.2 &&
      getTemporaryEffectEndTime(state, Wet.getId(), AttackTarget.Player) -
        getSecondsPlayed(state) >
        5
    ) {
      return {
        name: t("Frost"),
        delay: this.getAttackDelay(state) * 1.4,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.4,
            {
              tags: [SpellElement.Water],
              onHit: (state, result) => {
                if (result.isHit) {
                  state = grantTemporaryEffect(Chilled.getId(), 15, {
                    target: result.target,
                  })(state);
                }
                return state;
              },
            },
          );

          return state;
        },
      };
    }
    if (Math.random() < 0.2) {
      return {
        name: t("Splash"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.1,
            {
              tags: [SpellElement.Water],
              onHit: (state, result) => {
                if (result.isHit) {
                  state = grantTemporaryEffect(Wet.getId(), 15, {
                    target: result.target,
                  })(state);
                }
                return state;
              },
            },
          );

          return state;
        },
      };
    }
    if (Math.random() < 0.3 && getCurrentEnemyHPFraction(state) < 0.9) {
      return {
        name: t("Droplet of Healing"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          const healAmt = this.getMaxHP(state) * (0.02 + Math.random() * 0.005);
          state = modifyEnemyCurrentHP(state, healAmt);
          const actionResult = {
            id: Math.random().toString(),
            damage: -healAmt,
            isCrit: false,
            isHit: true,
            time: getSecondsPlayed(state),
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
          };
          state = pushCombatActionResult(state, actionResult);
          return state;
        },
      };
    }

    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  isBoss(): boolean {
    return true;
  }

  getBackdropColor(): string {
    return "#fd7";
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: "paper",
        amount: 3,
        chance: 1.0,
      },
    ];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = jumpEventToMessageIfExists(state, "finalTraining", "afterFight");
    state = setFlag(Flag.HeadInstructorDefeated)(state);
    return state;
  }

  getTips(): string {
    return t(
      "Ah, the Head Instructor of Combat! She is proficient in Water, Air and Fire spells, making her quite the force to be reckoned with. She will begin with Water spells only, and follow through with Fire and Air spells. Let's make sure to have solid buffs, such as from potions or spells, before the battle. Don't forget to ask me - well, us, your hired Wizards - to keep up your buffs for you, so you can focus on fighting!",
    );
  }
}

const headInstructor = new HeadInstructor();

registerAchievementUnlockerFromEnemy(
  headInstructor,
  Achievement.HEAD_INSTRUCTOR_GISELLE,
);

export default headInstructor;
