import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ActionEffect } from "../action/Action";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../transformation/TransformationTags";
import { CombatClass } from "./CombatClasses";

class Spellslinger extends CombatClass {
  getId(): string {
    return "spellslinger";
  }
  getDisplayName(): string {
    return t("Spellslinger");
  }
  protected getBaseClassEffects(): Record<string, ActionEffect> {
    return {
      mana: {
        value: 0.3,
        valueType: TransformationValueType.Percent,
      },
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "A wizard that simply loves to cast spells. Spells require Mana - a Spellslinger has mastered the ability to produce it.",
    );
  }
  getDisplayEffect(state: GameState): string {
    const effects = this.getClassEffects(state);
    const explanations = this.getClassEffectExplanations(state);
    return t("^{{mana}}^<{{manaExplanation}}> :mana: production", {
      mana: formatMultiplier(1.0 + effects.mana),
      manaExplanation: explanations.mana,
    });
  }
}

export const spellslinger = new Spellslinger();

registerTransformation(
  [[Resource.Mana, TransformationTags.Production]],
  spellslinger.getId(),
  spellslinger.getDisplayName(),
  TransformationType.Multiplier,
  (state) =>
    spellslinger.isActive(state)
      ? 1.0 + spellslinger.getClassEffects(state).mana
      : 1.0,
);
