import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { venomousBrew } from "../../../items/equipment/pouch/VenomousBrew";
import VenomPowder from "../../../items/material/VenomPowder";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { SpellElement } from "../../../spells/Elements";
import { poison } from "../../../temporaryeffects/Poison";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { weakened } from "../../../temporaryeffects/Weakened";
import {
  calculateAttackTarget,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { getLastQueuedActionResult } from "../../ExplorationUtils";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class VenomousSpider extends Enemy {
  getId(): string {
    return "venomousSpider";
  }

  getName(): string {
    return t("Venomous Spider");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 1100,
      attack: 57,
      defense: 10,
      attackDelay: 1.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./venomousSpider.png");
  }

  getLevel(): number {
    return 17;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 72;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 38;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (
      !hasTemporaryEffect(state, weakened.getId(), target) &&
      Math.random() < 0.3
    ) {
      return {
        name: t("Debilitating Venom"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.0,
            { tags: [SpellElement.Poison] },
          );
          const lastActionResult = getLastQueuedActionResult(state);
          if (lastActionResult.isHit) {
            state = grantTemporaryEffect(weakened.getId(), 18, {
              target: lastActionResult.target,
              params: {
                commonBuff: { AttackMultiplier: 0.7 },
              },
            })(state);
          }
          return state;
        },
      };
    }
    if (
      !hasTemporaryEffect(state, poison.getId(), target) &&
      Math.random() < 0.2
    ) {
      return {
        name: t("Sting"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.0,
            { tags: [SpellElement.Poison] },
          );
          const lastActionResult = getLastQueuedActionResult(state);
          if (lastActionResult.isHit) {
            state = grantTemporaryEffect(poison.getId(), 12, {
              target: lastActionResult.target,
              params: {
                magnitude: this.getAttack(state) / 12,
              },
            })(state);
          }
          return state;
        },
      };
    }
    if (Math.random() < 0.1) {
      return {
        name: t("Hop"),
        delay: this.getAttackDelay(state),
        transform: (state) => state,
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: VenomPowder.getId(),
        amount: 1,
        chance: 0.5,
      },
      {
        itemId: cotton.getId(),
        amount: 2,
        chance: 0.35,
      },
      {
        itemId: venomousBrew.getId(),
        amount: 1,
        chance: 0.01,
      },
    ];
  }
}

export default new VenomousSpider();
