import React from "react";
import {
  IconButton as PaperIconButton,
  IconButtonProps,
  useTheme,
} from "react-native-paper";

export function IconButton(props: IconButtonProps) {
  const theme = useTheme();
  const disabled = props.disabled ?? false;
  return (
    <PaperIconButton
      iconColor={
        disabled ? theme.colors.onSurfaceDisabled : theme.colors.onPrimary
      }
      containerColor={
        disabled ? theme.colors.surfaceDisabled : theme.colors.primary
      }
      {...props}
    />
  );
}
