import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import Wildgrass from "../../../items/material/Wildgrass";
import { SpellElement } from "../../../spells/Elements";
import { grantTemporaryEffect } from "../../../temporaryeffects/TemporaryEffects";
import { Wet } from "../../../temporaryeffects/Wet";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { pushCombatActionResult } from "../../ExplorationActionResult";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class AnimatedSnowman extends Enemy {
  getId(): string {
    return "animatedSnowman";
  }

  getName(): string {
    return t("Animated Snowman");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 1500,
      attack: 20,
      defense: 0,
      attackDelay: 3.0,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./animatedSnowman.png");
  }

  getLevel(): number {
    return 4;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 40;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 6;
  }

  getNextAction(state: GameState): BattlerAction {
    if (Math.random() < 0.1) {
      return {
        name: t("Splash"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 1.1,
            {
              tags: [SpellElement.Water],
              cause: t("Splash"),
              onHit: (state, result) => {
                if (result.isHit) {
                  state = grantTemporaryEffect(Wet.getId(), 15, {
                    target: result.target,
                  })(state);
                }
                return state;
              },
            },
          );

          return state;
        },
      };
    }
    if (Math.random() < 0.5) {
      return {
        name: t("Droplet of Healing"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: (state) => {
          const healAmt = this.getMaxHP(state) * (0.02 + Math.random() * 0.005);
          state = modifyEnemyCurrentHP(state, healAmt);
          const actionResult = {
            id: Math.random().toString(),
            damage: -healAmt,
            isCrit: false,
            isHit: true,
            time: getSecondsPlayed(state),
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
          };
          state = pushCombatActionResult(state, actionResult);
          return state;
        },
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  isBoss(): boolean {
    return true;
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 2,
        chance: 1.0,
      },
    ];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "trainingTowerAir");
    return state;
  }

  getTips(): string {
    return t(
      "Wow! The guardian of the Tower of Air Training looks like a snowman that is able to move on its own through some sort of magic. It has a lot of health, and is able to heal itself. That is annoying! On the other hand, attacks deal quite a bit of damage, even weak ones. Using Air Blast or other attack spells, a weak but speedy weapon, and having some way to heal like a few potions should do the trick!",
    );
  }
}

export default new AnimatedSnowman();
