import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import VenomPowder from "../../../items/material/VenomPowder";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { disease } from "../../../temporaryeffects/Disease";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import {
  calculateAttackTarget,
  drainingEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class SewerFly extends Enemy {
  getId(): string {
    return "sewerFly";
  }

  getName(): string {
    return t("Sewer Fly");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 420,
      attack: 37,
      defense: 10,
      attackDelay: 1.1,
      accuracy: 100,
      dodge: 120,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./sewerFly.png");
  }

  getLevel(): number {
    return 14;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 30;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 24;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (
      !hasTemporaryEffect(state, disease.getId(), target) &&
      Math.random() < 0.1
    ) {
      return {
        name: t("Diseased Leech"),
        delay: this.getAttackDelay(state) * 1.3,
        transform: (state: GameState) => {
          state = drainingEnemyAttackEffect(state, this.getAttack(state));
          state = grantTemporaryEffect(disease.getId(), 12, { target: target })(
            state,
          );
          return state;
        },
      };
    }
    if (Math.random() < 0.5) {
      return {
        name: t("Leech"),
        delay: this.getAttackDelay(state),
        transform: (state: GameState) => {
          state = drainingEnemyAttackEffect(state, this.getAttack(state));
          return state;
        },
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 1,
        chance: 0.55,
      },
      {
        itemId: cotton.getId(),
        amount: 1,
        chance: 0.13,
      },
      {
        itemId: VenomPowder.getId(),
        amount: 1,
        chance: 0.35,
      },
    ];
  }
}

export default new SewerFly();
