import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { ActionArea } from "../backend/action/ActionAreas";
import {
  getBoostsBonusText,
  hasBoosts,
} from "../backend/boost/BoostActionBase";
import { t } from "../utils/I18n";
import { ItemTile } from "./inventory/ItemTile";
import ActionsContainer from "./utility/ActionsContainer";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { useGameState } from "./utility/useGameState";

export default function Boosts() {
  const state = useGameState();
  const bonusText = getBoostsBonusText(state);
  const playerHasBoosts = hasBoosts(state);
  return useMemo(
    () => (
      <View style={styles.campusContainer}>
        <View style={Styles.flexRowAlignCenter}>
          <ItemTile icon="boosts" title={t("Boosts")} />
          <View style={Styles.flex}>
            <Markdown>
              {playerHasBoosts
                ? t(
                    "You are attuned to the Boosting Lamp. It is giving you the following bonuses: **{{bonusText}}**",
                    { bonusText },
                  )
                : t("You are attuned to the Boosting Lamp.")}
            </Markdown>
          </View>
        </View>
        <ActionsContainer area={ActionArea.BOOST} />
      </View>
    ),
    [playerHasBoosts, bonusText],
  );
}

const styles = StyleSheet.create({
  campusContainer: {
    height: "100%",
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
});
