import { fromEntries, keysOf, valuesOf } from "../../utils/CoreUtils";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasFlag } from "../Flags";
import {
  GameState,
  GameStateTransform,
  ResearcherAutoAssignStrategy,
} from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import {
  SpellElement,
  getUnlockedElements,
  grantElementExp,
} from "../spells/Elements";
import { registerTimeTickListener } from "../timetick/TimeTick";
import {
  applyTransformations,
  explainTransformationsText,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { decideOnAutoAssigning } from "./ResearcherAutoAssignStrategy";

function onTick(state: GameState, elapsedTimeSec: number): GameState {
  const expPerSec = getResearcherExpPerSecForElements(state);
  for (let element in expPerSec) {
    state = grantElementExp(
      element as SpellElement,
      (expPerSec?.[element as SpellElement] ?? 0) * elapsedTimeSec,
    )(state);
  }
  return state;
}

export const BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER = 2.0;

export const expGainTagsCache = fromEntries(
  valuesOf(SpellElement).map((element) => [
    element,
    [element, TransformationTags.ExpGain, TransformationTags.Study],
  ]),
);

export function getResearcherExpPerSecAndResearcher(
  state: GameState,
): Partial<Record<SpellElement, number>> {
  const researchRates: Partial<Record<SpellElement, number>> = {};
  for (let element of getUnlockedElements(state)) {
    const researchRatePerResearcher = applyTransformations(
      expGainTagsCache[element],
      state,
      BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER,
      { fromResearch: true },
    );
    researchRates[element] = researchRatePerResearcher;
  }
  return researchRates;
}

export function getResearcherExpPerSecForElements(
  state: GameState,
): Partial<Record<SpellElement, number>> {
  let value = state.temp.researchPerSecCache;
  if (value == null) {
    const researcherAllocation = state.run.studyAllocation;
    const totalExpPerSec: Partial<Record<SpellElement, number>> = {};
    for (let element of keysOf(researcherAllocation)) {
      const allocatedResearchers = researcherAllocation[element] || 0;
      if (allocatedResearchers > 0) {
        const researchRatePerResearcher = applyTransformations(
          expGainTagsCache[element],
          state,
          BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER,
          { fromResearch: true },
        );
        totalExpPerSec[element] =
          researchRatePerResearcher * allocatedResearchers;
      }
    }
    value = totalExpPerSec;
    state.temp.researchPerSecCache = value;
  }
  return value;
}

export function clearResearcherExpCache(state: GameState) {
  state.temp.researchPerSecCache = undefined;
  return state;
}

const expGainStudyTags = [TransformationTags.ExpGain, TransformationTags.Study];

export function getBaseResearchRate(state: GameState) {
  return applyTransformations(
    expGainStudyTags,
    state,
    BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER,
  );
}

export function explainBaseResearchRate(state: GameState) {
  return explainTransformationsText(
    expGainStudyTags,
    state,
    BASE_RESEARCH_EXP_PER_SEC_PER_RESEARCHER,
  );
}

const researcherAmountTags = [TransformationTags.ResearcherAmount];

export function getTotalResearchers(state: GameState) {
  return applyTransformations(researcherAmountTags, state, 1);
}

export function getFreeResearchers(state: GameState): number {
  const researcherAllocation = state.run.studyAllocation;
  let researchersAllocated = 0;
  for (let element of keysOf(researcherAllocation)) {
    researchersAllocated += researcherAllocation[element] || 0;
  }
  return getTotalResearchers(state) - researchersAllocated;
}

function allocateResearchersToElementImpl(
  element: SpellElement,
  amount: number,
  state: GameState,
) {
  const currentAllocation = state.run.studyAllocation[element] || 0;
  const actualAmountToAllocate =
    amount > 0
      ? Math.min(getFreeResearchers(state), amount)
      : Math.max(-currentAllocation, amount);
  state.run.studyAllocation[element] =
    currentAllocation + actualAmountToAllocate;
  if (amount < 0) {
    state.options.researcherAutoAssignStrategy =
      ResearcherAutoAssignStrategy.OFF;
  }
  state = clearResearcherExpCache(state);
  return state;
}

export function allocateResearchersToElement(
  this: any,
  element: SpellElement,
  amount: number,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return allocateResearchersToElementImpl.bind(this, element, amount);
}

export function isStudyUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.StudyUnlocked);
}

export function isStudyEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.StudyUnlocked);
}

export function loadResearchers(): void {
  registerTimeTickListener("Researchers", onTick);

  addGameStateListener("researcherAssignerOrRemover", (state) => {
    const freeResearchers = getFreeResearchers(state);
    if (freeResearchers < 0) {
      return (state: GameState) => {
        let freeResearchers = getFreeResearchers(state);
        const elements = getUnlockedElements(state);
        const elementLength = elements.length;
        let currentElementIdx = elementLength - 1;
        while (freeResearchers < 0 && currentElementIdx >= 0) {
          const element = elements[currentElementIdx];
          const assignedResearchers = state.run.studyAllocation[element];
          if (assignedResearchers > 0) {
            state = allocateResearchersToElement(element, -1)(state);
            freeResearchers++;
          } else {
            currentElementIdx--;
          }
        }
        return state;
      };
    } else if (
      freeResearchers > 0 &&
      state.options.researcherAutoAssignStrategy !=
        ResearcherAutoAssignStrategy.OFF
    ) {
      return (state: GameState) => {
        let freeResearchers = getFreeResearchers(state);
        while (freeResearchers > 0) {
          const elementToAssign = decideOnAutoAssigning(state);
          if (elementToAssign == null) {
            // Usually means all elements maxed
            state.options.researcherAutoAssignStrategy =
              ResearcherAutoAssignStrategy.OFF;
            return state;
          }
          state = allocateResearchersToElement(elementToAssign, 1)(state);
          freeResearchers--;
        }
        return state;
      };
    }
  });
}
