import mem from "mem";
import { formatMultiplier, formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { ActionEffect } from "../../action/Action";
import { AttackTarget } from "../../exploration/AttackTarget";
import { CombatStat } from "../../exploration/CombatStats";
import {
  IncomeOverTimeProducer,
  IncomeOverTimeProducers,
} from "../../timetick/IncomeOverTime";
import {
  TransformationType,
  registerTransformation,
} from "../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../transformation/TransformationTags";
import {
  EquipmentSlot,
  canEquipItem,
  equipToSlot,
  getEquippedItem,
  getEquippedItems,
} from "../Equipment";
import { Item, ItemAction, ItemOccurrence, ItemParams, ItemTag } from "../Item";
import { ItemTagEnum } from "../ItemTagEnum";
import { Items } from "../Items";

const memoizedEquip = mem(
  (
    canEquipItem: boolean,
    slot: EquipmentSlot,
    id: string,
    params: ItemParams,
  ) => {
    return {
      id: "equip",
      description: t("Equip"),
      isEnabled: canEquipItem,
      transform: (state: GameState) => {
        state = equipToSlot(state, slot, {
          itemId: id,
          params,
        });
        return state;
      },
    };
  },
  { cacheKey: JSON.stringify },
);

export abstract class EquippableItem extends Item {
  abstract getSlot(): EquipmentSlot;

  getTags(): ItemTag[] {
    return [ItemTagEnum.Equipment, this.getSlot() as unknown as ItemTagEnum];
  }

  getExtraSlots(): EquipmentSlot[] {
    return [];
  }

  getEquippedParams(state: GameState): ItemParams | undefined {
    const equippedItem = getEquippedItem(state, this.getSlot());
    if (equippedItem.itemId != this.getId()) {
      return;
    }
    return equippedItem.params;
  }

  getEquippedItemEffect(
    state: GameState,
    onlyId?: string,
  ): Record<string, number> {
    const equippedItem = getEquippedItems(state, this.getSlot()).find(
      (item) => item?.itemId == this.getId(),
    );
    if (equippedItem == null) {
      return;
    }
    return this.getItemEffects(state, equippedItem.params, onlyId);
  }

  getPrimaryAction(
    state: GameState,
    params: ItemParams,
  ): ItemAction | undefined {
    return memoizedEquip(
      canEquipItem(state, this.getSlot(), { itemId: this.getId(), params }),
      this.getSlot(),
      this.getId(),
      params,
    );
  }

  getAttackBonusBase(params: ItemParams): number {
    return 0;
  }

  attackTags = [CombatStat.Attack];

  getDefenseBonusBase(params: ItemParams): number {
    return 0;
  }

  defenseTags = [CombatStat.Defense];

  getMaxHPBonusBase(params: ItemParams): number {
    return 0;
  }

  maxHPTags = [CombatStat.MaxHP];

  getManaRegenBonusBase(params: ItemParams): number {
    return 0;
  }

  manaRegenTags = [Resource.Mana, TransformationTags.Production];

  getMaxManaBonusBase(params: ItemParams): number {
    return 0;
  }

  maxManaTags = [Resource.Mana, TransformationTags.PerItemCap];

  getAttackDelayBonusRatioBase(params: ItemParams): number {
    return 0;
  }

  attackDelayTags = [CombatStat.AttackDelay];

  getPlayerAccuracyBonusBase(params: ItemParams): number {
    return 0;
  }

  accuracyTags = [CombatStat.Accuracy];

  getPlayerDodgeBonusBase(params: ItemParams): number {
    return 0;
  }

  dodgeTags = [CombatStat.Dodge];

  getPlayerCritChanceBonusBase(params: ItemParams): number {
    return 0;
  }

  critTags = [CombatStat.Crit];

  protected getBaseExtraItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    return {};
  }

  protected getBaseItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    const bonuses: Record<string, ActionEffect> = {
      attack: {
        value: this.getAttackBonusBase(params),
        tags: this.attackTags,
        unit: t(":attack:"),
        valueType: TransformationValueType.Basic,
      },
      defense: {
        value: this.getDefenseBonusBase(params),
        tags: this.defenseTags,
        unit: t(":defense:"),
        valueType: TransformationValueType.Basic,
      },
      maxHP: {
        value: this.getMaxHPBonusBase(params),
        tags: this.maxHPTags,
        unit: t(" Max:hp:"),
        valueType: TransformationValueType.Basic,
      },
      manaRegen: {
        value: this.getManaRegenBonusBase(params),
        tags: this.manaRegenTags,
        unit: t(":mana:/sec"),
        valueType: TransformationValueType.Basic,
      },
      maxMana: {
        value: this.getMaxManaBonusBase(params),
        tags: this.maxManaTags,
        unit: t(" Max:mana:"),
        valueType: TransformationValueType.Basic,
      },
      attackDelay: {
        value: 1.0 / (1.0 + this.getAttackDelayBonusRatioBase(params)) - 1.0,
        tags: this.attackDelayTags,
        unit: t(":attackDelay:"),
        valueType: TransformationValueType.Divisor,
      },
      accuracy: {
        value: this.getPlayerAccuracyBonusBase(params),
        tags: this.accuracyTags,
        unit: t(":accuracy:"),
        valueType: TransformationValueType.Basic,
      },
      dodge: {
        value: this.getPlayerDodgeBonusBase(params),
        tags: this.dodgeTags,
        unit: t(":dodge:"),
        valueType: TransformationValueType.Basic,
      },
      crit: {
        value: this.getPlayerCritChanceBonusBase(params),
        tags: this.critTags,
        unit: t(":critChance:"),
        valueType: TransformationValueType.Basic,
      },
    };

    for (let key in bonuses) {
      if (bonuses[key].value == 0) {
        delete bonuses[key];
      }
    }

    const extra = this.getBaseExtraItemEffects(params);
    return {
      ...bonuses,
      ...extra,
    };
  }

  getEffect(state: GameState, params: ItemParams): string | undefined {
    return this.getEffectBase(state, params);
  }

  getEffectExtra(state: GameState, params: ItemParams): string | undefined {
    return undefined;
  }

  getEffectBase(state: GameState, params: ItemParams): string | undefined {
    const effects = this.getItemEffects(state, params);
    const effectExplanations = this.getItemEffectExplanations(state, params);

    const bonuses: Record<string, number> = {
      attack: effects.attack ?? 0,
      defense: effects.defense ?? 0,
      maxHP: effects.maxHP ?? 0,
      manaRegen: effects.manaRegen ?? 0,
      maxMana: effects.maxMana ?? 0,
      attackDelay: 1.0 / ((effects.attackDelay ?? 0) + 1.0),
      accuracy: effects.accuracy ?? 0,
      dodge: effects.dodge ?? 0,
      crit: effects.crit ?? 0,
    };

    const bonusStrings = Object.keys(bonuses).map((stat) => {
      let value = bonuses?.[stat];
      if (value == 0 || (value == 1.0 && stat == "attackDelay")) {
        return null;
      }
      const explanation = effectExplanations?.[stat];
      let prefix = "+";
      if (value < 0) {
        prefix = "-";
        value = Math.abs(value);
      }
      switch (stat) {
        case "attack":
        case "defense":
        case "accuracy":
        case "dodge":
          return t("^{{value}}^<{{explanation}}>{{statIcon}}", {
            value: prefix + formatNumber(value),
            explanation,
            statIcon: `:${stat}:`,
          });
        case "crit":
          return t("^{{value}}^<{{explanation}}>{{statIcon}}", {
            value: prefix + formatNumber(value),
            explanation,
            statIcon: `:critChance:`,
          });
        case "manaRegen":
          return t("^{{value}}^<{{explanation}}>:mana:/sec", {
            value: prefix + formatNumber(value),
            explanation,
          });
        case "maxHP":
          return t("^{{value}}^<{{explanation}}> Max:hp:", {
            value: prefix + formatNumber(value),
            explanation,
          });
        case "maxMana":
          return t("^{{value}}^<{{explanation}}> Max:mana:", {
            value: prefix + formatNumber(value),
            explanation,
          });
        case "attackDelay":
          return t("^{{value}}^<{{explanation}}>:attackDelay:", {
            value: formatMultiplier(value),
            explanation,
          });
      }
    });

    const bonusEffect = this.getEffectExtra(state, params);
    bonusStrings.push(bonusEffect);

    return bonusStrings
      .filter((bonusString) => bonusString != null)
      .join(t(", "));
  }

  isEquippedWithAnyParams(state: GameState): boolean {
    return getEquippedItems(state, this.getSlot()).some(
      (item) => item?.itemId == this.getId(),
    );
  }

  isItemQualityValid(): boolean {
    return true;
  }
}

type EquippedItemFunction = (itemOccurrence: ItemOccurrence) => void;

const forEachEquippedItem = function (
  state: GameState,
  each: EquippedItemFunction,
): void {
  const equippedItemHand = getEquippedItem(state, EquipmentSlot.Hand);
  const equippedItemBody = getEquippedItem(state, EquipmentSlot.Body);
  const equippedItemAccessory = getEquippedItems(
    state,
    EquipmentSlot.Accessory,
  );

  [equippedItemHand, equippedItemBody, ...equippedItemAccessory.flat()].forEach(
    (itemOccurrence) => {
      if (itemOccurrence == null) {
        return;
      }
      each(itemOccurrence);
    },
  );
};

type EquippedItemBonus = (
  state: GameState,
  item: EquippableItem,
  params: ItemParams,
) => number;

const additiveTransform = (bonusFn: EquippedItemBonus) => {
  return (state: GameState) => {
    let total = 0;
    forEachEquippedItem(state, (itemOccurrence) => {
      const item = Items.getById(itemOccurrence.itemId) as EquippableItem;

      const bonus = bonusFn(state, item, itemOccurrence.params);
      if (bonus != 0) {
        total += bonus;
      }
    });

    return total;
  };
};

const multiplicativeTransform = (bonusFn: EquippedItemBonus) => {
  return (state: GameState) => {
    let totalMultiplier = 1.0;
    forEachEquippedItem(state, (itemOccurrence) => {
      const item = Items.getById(itemOccurrence.itemId) as EquippableItem;

      const bonus = bonusFn(state, item, itemOccurrence.params);
      if (bonus != 1.0) {
        totalMultiplier *= bonus;
      }
    });

    return totalMultiplier;
  };
};

IncomeOverTimeProducers.register(
  new IncomeOverTimeProducer("EquippableItem", t("Equipped Items"), (state) => {
    const totalIncome: Partial<Record<Resource, number>> = {};
    forEachEquippedItem(state, (itemOccurrence) => {
      const item = Items.getById(itemOccurrence.itemId) as EquippableItem;

      const manaRegen = item.getItemEffect(
        state,
        itemOccurrence.params,
        "manaRegen",
        0,
      );
      if (manaRegen != 0) {
        totalIncome[Resource.Mana] =
          (totalIncome?.[Resource.Mana] ?? 0) + manaRegen;
      }
    });
    return totalIncome;
  }),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Attack]],
  "EquippableItemAttack",
  t("Equipped Items"),
  TransformationType.Addition,
  additiveTransform((state, item, params) =>
    item.getItemEffect(state, params, "attack", 0),
  ),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Defense]],
  "EquippableItemDefense",
  t("Equipped Items"),
  TransformationType.Addition,
  additiveTransform((state, item, params) =>
    item.getItemEffect(state, params, "defense", 0),
  ),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.MaxHP]],
  "EquippableItemMaxHP",
  t("Equipped Items"),
  TransformationType.Addition,
  additiveTransform((state, item, params) =>
    item.getItemEffect(state, params, "maxHP", 0),
  ),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.AttackDelay]],
  "EquippableItemAttackDelay",
  t("Equipped Items"),
  TransformationType.Multiplier,
  multiplicativeTransform(
    (state, item, params) =>
      1.0 / (item.getItemEffect(state, params, "attackDelay", 0) + 1.0),
  ),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Accuracy]],
  "EquippableItemAccuracy",
  t("Equipped Items"),
  TransformationType.Addition,
  additiveTransform((state, item, params) =>
    item.getItemEffect(state, params, "accuracy", 0),
  ),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Dodge]],
  "EquippableItemDodge",
  t("Equipped Items"),
  TransformationType.Addition,
  additiveTransform((state, item, params) =>
    item.getItemEffect(state, params, "dodge", 0),
  ),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Crit]],
  "EquippableItemCritChance",
  t("Equipped Items"),
  TransformationType.Addition,
  additiveTransform((state, item, params) =>
    item.getItemEffect(state, params, "crit", 0),
  ),
);

registerTransformation(
  [[Resource.Mana, TransformationTags.Cap]],
  "EquippableItemManaCap",
  t("Equipped Items"),
  TransformationType.Addition,
  (state) => {
    let capBonus = 0;
    forEachEquippedItem(state, (itemOccurrence) => {
      const item = Items.getById(itemOccurrence.itemId) as EquippableItem;

      const maxManaBonus = item.getItemEffect(
        state,
        itemOccurrence.params,
        "maxMana",
        0,
      );
      if (maxManaBonus != 0) {
        capBonus += maxManaBonus;
      }
    });

    return capBonus;
  },
);
