export const DEFAULT_SECTION_ORDER = getAllSections().map(
  (sectionData) => sectionData.id,
);

export function getAllMenuItems(state: GameState): string[] {
  return getAllSections()
    .filter((section) => section.isVisible == null || section.isVisible(state))
    .map((section) => section.id) as string[];
}

export function getSectionInfo(section: string): SectionData {
  return getAllSections().find((sectionInfo) => sectionInfo.id == section);
}

export function useHorizontalScrollRef() {
  const elRef = useRef<any>();
  useEffect(() => {
    if (Platform.OS !== "web") {
      return;
    }
    const el = elRef.current;
    if (el) {
      const onWheel = (e: any) => {
        if (e.deltaY === 0) return;
        const scrollableElement = el.getScrollableNode();
        if (
          !(scrollableElement.scrollLeft === 0 && e.deltaY < 0) &&
          !(
            scrollableElement.scrollWidth -
              scrollableElement.clientWidth -
              Math.round(scrollableElement.scrollLeft) ===
              0 && e.deltaY > 0
          )
        ) {
          e.preventDefault();
          e.stopPropagation();
        }
        scrollableElement.scrollBy({ left: e.deltaY, behavior: "smooth" });
      };
      el.getScrollableNode().addEventListener("wheel", onWheel);
      return () =>
        el.getScrollableNode()?.removeEventListener?.("wheel", onWheel);
    }
  }, []);
  return elRef;
}

export const MAX_OPEN_TABS = 5;

export function useLayoutType() {
  const { width, height } = useWindowDimensions();
  const state = useGameState();
  return getLayoutType(state, height, width);
}

export function getLayoutType(state: GameState, height: number, width: number) {
  if (width < 700) {
    return {
      wideLayout: false,
      showMenu: false,
      showSpellMenu: false,
      showEventsMenu: false,
      activeContentTabs: 1,
    };
  }
  if (width < 1000) {
    return {
      wideLayout: true,
      showMenu: true,
      showSpellMenu: false,
      showEventsMenu: false,
      activeContentTabs: 1,
    };
  }
  if (width < 1350) {
    return {
      wideLayout: true,
      showMenu: true,
      showSpellMenu: true,
      showEventsMenu: false,
      activeContentTabs: 1,
    };
  }
  return {
    wideLayout: true,
    showMenu: true,
    showSpellMenu: true,
    showEventsMenu: !state?.options?.doNotShowEventsOnWideScreens,
    activeContentTabs: Math.min(
      MAX_OPEN_TABS,
      Math.max(
        1,
        Math.floor(
          (width -
            300 *
              (2 + (state?.options?.doNotShowEventsOnWideScreens ? 0 : 1))) /
            480,
        ),
      ),
    ),
  };
}

export function getInitialActiveSectionObjects(
  state: GameState,
): { section: string; key: number }[] {
  const tabCount = MAX_OPEN_TABS;
  const menuItems = getAllMenuItems(state);
  return menuItems
    .slice(0, tabCount)
    .map((menuItem, idx) => ({ section: menuItem, key: idx }));
}

