import { FamiliarClass } from "./FamiliarClass";

export enum FamiliarStatusType {
  Idle = "Idle",
  Exploring = "Exploring",
  Companion = "Companion",
}

export function translateFamiliarStatusType(
  status: FamiliarStatusType,
): string {
  switch (status) {
    case FamiliarStatusType.Idle:
      return t("Idle");
    case FamiliarStatusType.Exploring:
      return t("Exploring");
    case FamiliarStatusType.Companion:
      return t("At your side");
    // Do not add a default
  }
}

export enum FamiliarCompanionStatusType {
  Reviving = "Reviving",
  Exploring = "Exploring",
  Combat = "Combat",
  Preparing = "Preparing",
}

export function translateFamiliarCompanionStatusType(
  status: FamiliarCompanionStatusType,
): string {
  switch (status) {
    case FamiliarCompanionStatusType.Preparing:
      return t("Getting ready");
    case FamiliarCompanionStatusType.Exploring:
      return t("Exploring");
    case FamiliarCompanionStatusType.Reviving:
      return t("Recovering");
    case FamiliarCompanionStatusType.Combat:
      return t("Fighting");
    // Do not add a default
  }
}

export type FamiliarStatus = {
  status: FamiliarStatusType;
  exploringDungeonFloorId: string;
  exploringNextRandomRoll: number;
  autoExploring: boolean;
  currentHp: number;
  triggerFamiliarDeath: boolean;
  actionProgress: number;
  companionStatus: FamiliarCompanionStatusType;
};

export type FamiliarData = {
  id: string;
  familiarClassId: string;
  exp: number;
  status: FamiliarStatus;
  messageLog: MessageLog[];
};

export function getFamiliarData(
  state: GameState,
  familiarId: string,
): FamiliarData | undefined {
  return state.run.familiars?.[familiarId];
}

export const FamiliarDeletionListeners: DataStore<GameStateTransform> =
  new DataStore();

export function deleteFamiliar(
  state: GameState,
  familiarId: string,
): GameState {
  delete state.run.familiars[familiarId];
  FamiliarDeletionListeners.getAll().forEach((transform) => {
    state = transform(state);
  });
  return state;
}

export function getCurrentCompanionId(state: GameState): string {
  return getCurrentCompanionData(state)?.id;
}

export function getCurrentCompanionData(
  state: GameState,
): FamiliarData | undefined {
  return valuesOf(state.run.familiars).find(
    (familiarData) =>
      familiarData.status.status === FamiliarStatusType.Companion,
  );
}

export function getCurrentCompanionClass(
  state: GameState,
): FamiliarClass | undefined {
  const companionData = getCurrentCompanionData(state);
  if (companionData == null) {
    return undefined;
  }

  return Familiars.getById(companionData.familiarClassId);
}

export function setCurrentCompanion(
  state: GameState,
  familiarId: string,
): GameState {
  const currentCompanion = getCurrentCompanionData(state);
  if (currentCompanion != null) {
    currentCompanion.status.status = FamiliarStatusType.Idle;
  }

  const newCompanionData = getFamiliarData(state, familiarId);
  if (newCompanionData == null) {
    return state;
  }

  newCompanionData.status.status = FamiliarStatusType.Companion;
  newCompanionData.status.companionStatus =
    FamiliarCompanionStatusType.Preparing;
  newCompanionData.status.actionProgress = 0;

  return state;
}

export function setFamiliarToExplore(
  state: GameState,
  familiarId: string,
  dungeonFloorId: string,
): GameState {
  const familiarData = getFamiliarData(state, familiarId);
  if (familiarData == null) {
    return state;
  }

  familiarData.status.status = FamiliarStatusType.Exploring;
  familiarData.status.actionProgress = 0;
  familiarData.status.exploringDungeonFloorId = dungeonFloorId;
  familiarData.status.autoExploring = false;
  familiarData.status.exploringNextRandomRoll = Math.random();

  return state;
}

export function setAllFamiliarsToExplore(
  state: GameState,
  dungeonFloorId: string,
): GameState {
  for (let familiarId of keysOf(state.run.familiars)) {
    const familiarData = getFamiliarData(state, familiarId);
    if (
      familiarData == null ||
      familiarData.status.status == FamiliarStatusType.Companion
    ) {
      continue;
    }
    state = setFamiliarToExplore(state, familiarId, dungeonFloorId);
  }

  return state;
}

export function setFamiliarToAutoExplore(
  state: GameState,
  familiarId: string,
): GameState {
  const familiarData = getFamiliarData(state, familiarId);
  if (familiarData == null) {
    return state;
  }

  familiarData.status.status = FamiliarStatusType.Exploring;
  familiarData.status.actionProgress = 0;
  familiarData.status.exploringDungeonFloorId = findMaxFloorForFamiliar(
    state,
    familiarId,
  ).getId();
  familiarData.status.autoExploring = true;
  familiarData.status.exploringNextRandomRoll = Math.random();

  return state;
}

export function setAllFamiliarsToAutoExplore(state: GameState): GameState {
  for (let familiarId of keysOf(state.run.familiars)) {
    const familiarData = getFamiliarData(state, familiarId);
    if (
      familiarData == null ||
      familiarData.status.status == FamiliarStatusType.Companion
    ) {
      continue;
    }
    state = setFamiliarToAutoExplore(state, familiarId);
  }

  return state;
}

export function setIdle(state: GameState, familiarId: string): GameState {
  const familiarData = getFamiliarData(state, familiarId);
  if (familiarData == null) {
    return state;
  }

  familiarData.status.status = FamiliarStatusType.Idle;
  return state;
}

export function getFamiliarAmount(state: GameState): number {
  return keysOf(state.run.familiars).length;
}

export function getFamiliarMessageLog(
  state: GameState,
  familiarId: string,
): MessageLog[] {
  return getFamiliarData(state, familiarId)?.messageLog;
}

let throttleTimestamps: Record<string, number> = {};
let throttleCounts: Record<string, number> = {};

const THROTTLE_THRESHOLD = 40;

export function wouldBeThrottledIfPushingToCreatureMessageLog(
  state: GameState,
  familiarId: string,
) {
  const timestamp = Date.now();
  const timestampSec = Math.floor(timestamp / 1000);
  const throttleTimestamp = throttleTimestamps?.[familiarId] ?? 0;
  const throttleCount = throttleCounts?.[familiarId] ?? 0;
  return (
    timestampSec == throttleTimestamp && throttleCount >= THROTTLE_THRESHOLD
  );
}

export function pushToFamiliarMessageLog(
  state: GameState,
  familiarId: string,
  log: string,
): GameState {
  const timestamp = Date.now();
  const timestampSec = Math.floor(timestamp / 1000);
  const throttleTimestamp = throttleTimestamps?.[familiarId] ?? 0;
  let throttleCount = throttleCounts?.[familiarId] ?? 0;
  if (throttleTimestamp == timestampSec) {
    throttleCounts[familiarId] = throttleCount++;
  } else {
    throttleTimestamps[familiarId] = timestampSec;
    throttleCounts[familiarId] = 1;
  }
  const familiarData = getFamiliarData(state, familiarId);
  if (familiarData == null) {
    return state;
  }
  const familiarMessageLog = familiarData.messageLog;
  if (throttleCount == THROTTLE_THRESHOLD) {
    familiarMessageLog.unshift({
      id: Math.random().toString(),
      secondsPlayed: getSecondsPlayed(state),
      realTimestamp: timestamp,
      message: t("(More loot omitted)"),
    });
  } else if (throttleCount < THROTTLE_THRESHOLD) {
    familiarMessageLog.unshift({
      id: Math.random().toString(),
      secondsPlayed: getSecondsPlayed(state),
      realTimestamp: timestamp,
      message: log,
    });
  }
  familiarData.messageLog = familiarMessageLog.slice(0, 40);
  return state;
}

export function areFamiliarsUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.FamiliarsUnlocked);
}

export function areFamiliarsEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.FamiliarsUnlocked);
}

export function isDungeonFloorValidForFamiliars(
  state: GameState,
  dungeonFloorId: string,
): boolean {
  const dungeonFloor = DungeonFloors.getById(dungeonFloorId);

  if (!dungeonFloor.isUnlocked(state)) {
    return false;
  }

  if (
    !dungeonFloor.canFightBoss(state) &&
    dungeonFloor.getBoss(state) !== undefined
  ) {
    return false;
  }

  return true;
}

export function getFloorsAvailableForFamiliars(state: GameState) {
  if (!areFamiliarsUnlocked(state)) {
    return [];
  }
  return cacheIndefinitely(
    state,
    (_state) => "floorsAvailableForFamiliars",
    (state) => {
      const dungeons = getUnlockedDungeons(state);
      const floors = dungeons
        .map((dungeon) => {
          return dungeon
            .getDungeonFloors()
            .filter((floor) =>
              isDungeonFloorValidForFamiliars(state, floor.getId()),
            );
        })
        .flat();
      return floors;
    },
  );
}

const Familiars = new DataStore<FamiliarClass>();

export { Familiars };

const familiarExplorationDelayTags = [
  TransformationTags.FamiliarExplorationDelay,
];

function calculateExplorationDelaySec(
  state: GameState,
  familiarId: string,
): number {
  const familiarData = getFamiliarData(state, familiarId);
  const familiarClass = Familiars.getById(familiarData.familiarClassId);
  const dungeonFloor = DungeonFloors.getById(
    familiarData.status.exploringDungeonFloorId,
  );
  const explorationResult = dungeonFloor.doExplore(
    state,
    true,
    familiarData.status.exploringNextRandomRoll,
  );
  const enemy = explorationResult.enemy;
  const familiarLevel = familiarClass.getLevel(state, familiarId);
  const enemyLevel = enemy?.getLevel() ?? familiarLevel;
  const overlevel = Math.max(0, familiarLevel - enemyLevel);
  const baseExplorationTime = applyTransformationsCached(
    familiarExplorationDelayTags,
    state,
    30.0,
  );
  const explorationTime = Math.pow(0.94, overlevel) * baseExplorationTime;
  return explorationTime;
}

function findMaxFloorForFamiliar(
  state: GameState,
  familiarId: string,
): DungeonFloor | undefined {
  return cacheByArbitraryVersion(
    state,
    "maxFloorForFamiliarId" + familiarId,
    (state) => {
      const floors = getFloorsAvailableForFamiliars(state);
      floors.sort(
        (a, b) =>
          b.calculateAverageEnemyLevelForFamiliars(state) -
          a.calculateAverageEnemyLevelForFamiliars(state),
      );
      const topFloor = floors.filter(
        (floor) => !floor.isDangerousForFamiliar(state, familiarId),
      )[0];
      return topFloor;
    },
    (state) => {
      const familiarData = getFamiliarData(state, familiarId);
      const familiarClass = Familiars.getById(familiarData.familiarClassId);
      const level = familiarClass.getLevel(state, familiarId);

      const floors = getFloorsAvailableForFamiliars(state);

      return level.toString() + floors.length.toString();
    },
  );
}

export function loadFamiliarCommonLogic() {
  registerTransformation(
    [[TransformationTags.FamiliarExp]],
    "familiarExpScalingFromEnemyLevel",
    t("Strong Enemy"),
    TransformationType.Multiplier,
    (state, params) => {
      const enemyId = params.enemyId;
      const familiarId = params.familiarId;

      if (familiarId == null) {
        return 1.0;
      }

      const enemyLevel = Enemies.getById(enemyId).getLevel();
      const familiarClass = Familiars.getById(
        getFamiliarData(state, familiarId).familiarClassId,
      );
      const familiarLevel = familiarClass.getLevel(state, familiarId);
      const levelDifferenceFactor = Math.max(
        0,
        Math.min(10, enemyLevel - familiarLevel),
      );

      return Math.pow(1.2, levelDifferenceFactor);
    },
  );

  const familiarHPRegenTags = [CombatStat.HPRegen, AttackTarget.Familiar];
  registerTimeTickListener(
    "Familiars",
    (state, timeElapsedSec, isWarp) => {
      for (let familiarId in state.run.familiars) {
        const familiarData = getFamiliarData(state, familiarId);
        const familiarClass = Familiars.getById(familiarData.familiarClassId);

        // Apply HP regen: fully recover in 2 minutes normally,
        // but boost HP regen by 10x outside of combat for the current
        // companion
        const regenFraction =
          (timeElapsedSec / 120.0) *
          (getExplorationStatus(state) != ExplorationStatus.Combat &&
          familiarData.status.status == FamiliarStatusType.Companion
            ? 10.0
            : 1.0);
        const maxHP = familiarClass.getMaxHP(state, familiarId);
        const hpToRegenMultiplier = getCachedCombatStat(
          state,
          AttackTarget.Familiar,
          CombatStat.HPRegen,
          (state) => applyTransformations(familiarHPRegenTags, state, maxHP),
          familiarId,
          familiarClass.getLevel(state, familiarId),
        );
        familiarData.status.currentHp = Math.min(
          familiarData.status.currentHp + hpToRegenMultiplier * regenFraction,
          maxHP,
        );

        if (familiarData.status.status == FamiliarStatusType.Exploring) {
          // We will need to make progress here, AKA breeding-style
          const dungeonFloor = DungeonFloors.getById(
            familiarData.status.exploringDungeonFloorId,
          );
          let explorationResult = dungeonFloor.doExplore(
            state,
            true,
            familiarData.status.exploringNextRandomRoll,
          );
          let explorationTime = calculateExplorationDelaySec(state, familiarId);
          const progress = Math.min(1.0, timeElapsedSec / explorationTime);
          let totalProgress = progress + familiarData.status.actionProgress;
          while (totalProgress >= 1.0) {
            // Explore
            totalProgress -= 1.0;
            state = explorationResult.familiar(state, familiarId);
            const overflowProgress = totalProgress;
            familiarData.status.exploringNextRandomRoll = Math.random();

            // If auto-exploring, check highest-level floor
            // and switch dungeons if desired
            if (familiarData.status.autoExploring) {
              const nextFloor = findMaxFloorForFamiliar(state, familiarId);
              familiarData.status.exploringDungeonFloorId = nextFloor.getId();
            }

            const newExplorationTime = calculateExplorationDelaySec(
              state,
              familiarId,
            );
            // Adapt the overflow progress
            totalProgress =
              (overflowProgress * explorationTime) / newExplorationTime;
            explorationResult = dungeonFloor.doExplore(
              state,
              true,
              familiarData.status.exploringNextRandomRoll,
            );
            explorationTime = newExplorationTime;
          }
          familiarData.status.actionProgress = totalProgress;
        }
      }
      return state;
    },
    -20,
  );

  addGameStateListener(
    "FamiliarCombatEnd",
    (state) => {
      const companion = getCurrentCompanionData(state);
      if (companion != null) {
        if (
          (getExplorationStatus(state) === ExplorationStatus.Exploring ||
            getExplorationStatus(state) === ExplorationStatus.ExploringBoss) &&
          (companion.status.companionStatus ===
            FamiliarCompanionStatusType.Combat ||
            companion.status.companionStatus ===
              FamiliarCompanionStatusType.Preparing)
        ) {
          companion.status.companionStatus =
            FamiliarCompanionStatusType.Exploring;
          state = clearCombatStatCache(state, AttackTarget.Familiar);
        }

        if (
          getExplorationStatus(state) === ExplorationStatus.Combat &&
          companion.status.companionStatus ===
            FamiliarCompanionStatusType.Exploring
        ) {
          companion.status.companionStatus = FamiliarCompanionStatusType.Combat;
          state = clearCombatStatCache(state, AttackTarget.Familiar);
        }
      }
    },
    (state) => getExplorationStatus(state),
  );

  addGameStateListener("FamiliarRevived", (state) => {
    const companion = getCurrentCompanionData(state);
    if (companion != null) {
      if (
        companion.status.companionStatus ===
          FamiliarCompanionStatusType.Reviving &&
        companion.status.currentHp >=
          getCurrentCompanionClass(state).getStatsForCompanion(state).maxHP *
            0.999
      ) {
        // Reviving is done and ready to go!
        switch (getExplorationStatus(state)) {
          case ExplorationStatus.Exploring:
          case ExplorationStatus.ExploringBoss:
          case ExplorationStatus.Reviving:
          case ExplorationStatus.None:
            companion.status.companionStatus =
              FamiliarCompanionStatusType.Exploring;
          case ExplorationStatus.Combat:
            companion.status.companionStatus =
              FamiliarCompanionStatusType.Combat;
        }
        state = clearCombatStatCache(state, AttackTarget.Familiar);
      }
    }
  });
}
