import Constants from "expo-constants";
import React, { useRef } from "react";
import { StyleSheet, View } from "react-native";
import { isDemo } from "../backend/Demo";
import { addUICommand } from "../backend/UICommands";
import {
  getDemoAboutText,
  getMR1AboutText,
} from "../backend/events/DemoEndEvent";
import { t } from "../utils/I18n";
import {
  getCurrentVersion,
  getCurrentVersionTimestamp,
} from "../utils/Versioning";
import { Button } from "./utility/Button";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

export default function About() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const versionText = isDemo() ? t("**Demo Version**") : t("**Full Version**");
  const versionNumber = getCurrentVersionTimestamp() ?? t("Unknown");
  const versionString = getCurrentVersion() ?? t("Unknown");
  const versionNumberText = t(`Version {{versionString}} ({{versionNumber}})`, {
    versionString,
    versionNumber,
  });
  const demoText = isDemo() ? getDemoAboutText() : "";
  const mr1text = getMR1AboutText();
  const text = t(
    `## Magic Research 2

  
{{versionText}}

{{versionNumberText}}


A game by **@Maticolotto**


**Credits**

Assets from:
  * Raven Fantasy Icons, by Clockwork Raven [(https://clockworkraven.itch.io/raven-fantasy-icons)](https://clockworkraven.itch.io/raven-fantasy-icons)
  * Aekashics Librarium, by Aekashics [(http://www.akashics.moe)](http://www.akashics.moe)
  * 1000+ Fantasy RPG Icons, by finalbossblues [(https://finalbossblues.itch.io/icons)](https://finalbossblues.itch.io/icons)

Special thanks to **@jpascua93** for pre-launch feedback. development, and marketing assets

Special thanks to: **@Wiedmolol**, **@lunacent**, **@Fich**, **@Falk**, **@Aegys**, **@UVGamma**, **@AbsolFan1**, **@Kolt** for pre-launch feedback

Check out the **[Discord](https://discord.gg/bPhGsaqR9d)** to discuss the game!

[Privacy Policy](https://mcolotto.github.io/magic-research-demo/privacy-policy)

{{demoText}}

{{mr1text}}`,
    { versionText, versionNumberText, demoText, mr1text },
  );

  return (
    <View style={styles.container}>
      <Text h1>{t("About")}</Text>
      <View style={styles.explanation}>
        <Markdown style={styles.explanationText}>{text}</Markdown>
        {Constants?.expoConfig?.extra?.showDebug && (
          <Button
            onPress={() => {
              stateRef.current.apply((state) => {
                state = addUICommand(state, "goToDebug");
                return state;
              });
            }}
            style={Styles.mt16}
          >
            Show debug
          </Button>
        )}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  explanation: {
    marginTop: 16,
    flex: 1,
    alignItems: "stretch",
    width: "100%",
  },
  explanationText: {
    width: "100%",
  },
});
