import React from "react";
import { AttackTarget } from "../../backend/exploration/AttackTarget";
import { getCurrentEnemyHP } from "../../backend/exploration/Exploration";
import { ExplorationStatus } from "../../backend/exploration/ExplorationStatus";
import { getExplorationStatus } from "../../backend/exploration/ExplorationUtils";
import { getCurrentEnemy } from "../../backend/exploration/enemies/Enemies";
import { useGameState } from "../utility/useGameState";
import { HPBar } from "./HPBar";

export function EnemyHPBar() {
  const state = useGameState();
  const inCombat = getExplorationStatus(state) == ExplorationStatus.Combat;
  let currentHP = 0;
  let maxHP = 1;
  if (inCombat) {
    currentHP = getCurrentEnemyHP(state);
    maxHP = getCurrentEnemy(state).getMaxHP(state);
  }

  return (
    <HPBar
      empty={!inCombat}
      current={currentHP}
      max={maxHP}
      actionTarget={AttackTarget.Enemy}
    />
  );
}
