import React, { useCallback, useContext, useMemo, useRef } from "react";
import {
  Platform,
  ScrollView,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
  useWindowDimensions,
} from "react-native";
import { useTheme } from "react-native-paper";
import { useSafeAreaInsets } from "react-native-safe-area-context";
import { clearFlag, hasFlag, setFlag } from "../../backend/Flags";
import { GameState } from "../../backend/GameState";
import { ActionArea } from "../../backend/action/ActionAreas";
import { t } from "../../utils/I18n";
import SpellsContainer from "../spells/SpellsContainer";
import { AccessibilityContext } from "../utility/AccessibilityContext";
import { GameStateFreezer } from "../utility/GameStateFreezer";
import { KeyboardListener } from "../utility/KeyboardListener";
import { Text } from "../utility/Text";
import { UIContext, useUIStateFromContext } from "../utility/UIContext";
import { VisibilityContext } from "../utility/VisibilityContext";
import { useGameState } from "../utility/useGameState";
import ExplorationPouch from "./ExplorationPouch";
import { QuickbarContainer } from "./QuickbarContainer";
import { StrategyContainer } from "./StrategyContainer";

export function ExplorationBottomComponent(props: { containerHeight: number }) {
  const uiContext = useContext(UIContext);
  const [openedTab, setOpenedTab] = useUIStateFromContext(
    uiContext,
    "exploration_bottom_component_tab",
    "",
  );
  const theme = useTheme();
  const visible = useContext(VisibilityContext);
  const { height, width } = useWindowDimensions();
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const isBottomMenuEnabled = state.options.enableBottomMenu;

  const insets = useSafeAreaInsets();

  const accessibilityContext = useContext(AccessibilityContext);
  const isPaused = hasFlag(state, "combat_paused_magic_tap");

  const onPause = useCallback(() => {
    stateRef.current.apply((state: GameState) =>
      isPaused
        ? clearFlag("combat_paused_magic_tap")(state)
        : setFlag("combat_paused_magic_tap")(state),
    );
    accessibilityContext.announceForAccessibility(
      isPaused ? t("Resuming Exploration") : t("Pausing Exploration"),
    );
  }, [accessibilityContext, isPaused]);

  const onKeyboard = useCallback(
    (ev: KeyboardEvent) => {
      if (visible && ev.shiftKey && ev.ctrlKey && ev.key == "X") {
        onPause();
      }
    },
    [onPause, visible],
  );

  return useMemo(
    () => (
      <View>
        <View
          style={[
            styles.mainComponent,
            {
              backgroundColor: theme.colors.secondaryContainer,
              borderColor: theme.colors.outlineVariant,
            },
            !openedTab && { display: "none" },
          ]}
        >
          {openedTab == "Inventory" && (
            <ScrollView
              style={[
                styles.mainScrollView,
                {
                  height: Math.max(
                    Math.min(props.containerHeight - 434, 350),
                    168,
                  ),
                },
              ]}
              nestedScrollEnabled={true}
            >
              <View
                style={styles.accessibilityHidden}
                accessible={true}
                accessibilityLabel={t("Inventory Tab")}
                accessibilityRole="header"
                accessibilityElementsHidden={false}
              />
              <ExplorationPouch />
            </ScrollView>
          )}
          <ScrollView
            style={[
              styles.mainScrollView,
              {
                height: Math.max(
                  Math.min(props.containerHeight - 434, 350),
                  168,
                ),
              },
              openedTab != "Spells" && { display: "none" },
            ]}
          >
            <View
              style={styles.accessibilityHidden}
              accessible={true}
              accessibilityLabel={t("Spells Tab")}
              accessibilityRole="header"
              accessibilityElementsHidden={false}
            />
            <GameStateFreezer isVisible={openedTab == "Spells"}>
              <SpellsContainer
                uiKey="ExplorationBottomContainer"
                area={ActionArea.EXPLORATION}
                hideRecent={true}
                showAddToQuickbar={true}
                onlyBySchool={true}
                hideTitle={true}
                isVisible={openedTab == "Spells"}
                dividerColor={theme.colors.surfaceDisabled}
              />
            </GameStateFreezer>
          </ScrollView>
          {openedTab == "Quickbar" && (
            <View
              style={[
                styles.quickbarScrollView,
                {
                  height: Math.max(
                    Math.min(props.containerHeight - 434, 350),
                    168,
                  ),
                },
              ]}
            >
              <View
                style={styles.accessibilityHidden}
                accessible={true}
                accessibilityLabel={t("Quickbar Tab")}
                accessibilityRole="header"
                accessibilityElementsHidden={false}
              />
              <QuickbarContainer />
            </View>
          )}
          {openedTab == "Strategy" && (
            <ScrollView
              style={[
                styles.mainScrollView,
                {
                  height: Math.max(
                    Math.min(props.containerHeight - 434, 350),
                    150,
                  ),
                },
                openedTab != "Strategy" && { display: "none" },
              ]}
            >
              <View
                style={styles.accessibilityHidden}
                accessible={true}
                accessibilityLabel={t("Strategy Tab")}
                accessibilityRole="header"
                accessibilityElementsHidden={false}
              />
              <StrategyContainer />
            </ScrollView>
          )}
        </View>
        <View style={styles.tabsContainer}>
          <View
            style={styles.accessibilityHidden}
            accessible={true}
            accessibilityLabel="Exploration Tabs"
            accessibilityRole="header"
            accessibilityElementsHidden={false}
          />
          <TouchableWithoutFeedback
            onPress={onPause}
            accessibilityElementsHidden={false}
          >
            <View
              accessibilityLabel={
                isPaused
                  ? t("Resume Exploration {{shortcut}}", {
                      shortcut:
                        Platform.OS === "web"
                          ? t("(Shortcut Control + Shift + X)")
                          : "",
                    })
                  : t("Pause Exploration {{shortcut}}", {
                      shortcut:
                        Platform.OS === "web"
                          ? t("(Shortcut Control + Shift + X)")
                          : "",
                    })
              }
              accessibilityRole="button"
              style={[styles.accessibilityHidden, { left: 1 }]}
            />
          </TouchableWithoutFeedback>
          <TouchableWithoutFeedback
            onPress={() => {
              setOpenedTab("");
            }}
            accessibilityElementsHidden={false}
          >
            <View
              accessibilityLabel={t("Exploration Log - Exploration")}
              accessibilityRole="tab"
              style={[styles.accessibilityHidden, { left: 2 }]}
            />
          </TouchableWithoutFeedback>
          <KeyboardListener callback={onKeyboard} />
          <TouchableWithoutFeedback
            onPress={() => {
              if (openedTab != "Inventory") {
                setOpenedTab("Inventory");
              } else {
                setOpenedTab("");
              }
            }}
            accessibilityLabel={t("Items - Exploration")}
            accessibilityRole="tab"
          >
            <View
              style={[
                styles.tab,
                {
                  backgroundColor: theme.colors.secondaryContainer,
                  borderColor: theme.colors.outlineVariant,
                },
                openedTab == "Inventory" && styles.selectedTab,
              ]}
            >
              <Text h3>{t("Items")}</Text>
            </View>
          </TouchableWithoutFeedback>
          <TouchableWithoutFeedback
            onPress={() => {
              if (openedTab != "Spells") {
                setOpenedTab("Spells");
              } else {
                setOpenedTab("");
              }
            }}
            accessibilityLabel={t("Spells - Exploration")}
            accessibilityRole="tab"
          >
            <View
              style={[
                styles.tab,
                {
                  backgroundColor: theme.colors.secondaryContainer,
                  borderColor: theme.colors.outlineVariant,
                },
                openedTab == "Spells" && styles.selectedTab,
              ]}
            >
              <Text h3>{t("Spells")}</Text>
            </View>
          </TouchableWithoutFeedback>
          <TouchableWithoutFeedback
            onPress={() => {
              if (openedTab != "Strategy") {
                setOpenedTab("Strategy");
              } else {
                setOpenedTab("");
              }
            }}
            accessibilityLabel={t("Strategy - Exploration")}
            accessibilityRole="tab"
          >
            <View
              style={[
                styles.tab,
                {
                  backgroundColor: theme.colors.secondaryContainer,
                  borderColor: theme.colors.outlineVariant,
                },
                openedTab == "Strategy" && styles.selectedTab,
              ]}
            >
              <Text h3>{t("Strategy")}</Text>
            </View>
          </TouchableWithoutFeedback>
          <TouchableWithoutFeedback
            onPress={() => {
              if (openedTab != "Quickbar") {
                setOpenedTab("Quickbar");
              } else {
                setOpenedTab("");
              }
            }}
            accessibilityLabel={t("Quickbar - Exploration")}
            accessibilityRole="tab"
          >
            <View
              style={[
                styles.tab,
                {
                  backgroundColor: theme.colors.secondaryContainer,
                  borderColor: theme.colors.outlineVariant,
                },
                openedTab == "Quickbar" && styles.selectedTab,
              ]}
            >
              <Text h3>{t("Quickbar")}</Text>
            </View>
          </TouchableWithoutFeedback>
        </View>
      </View>
    ),
    [
      openedTab,
      setOpenedTab,
      theme,
      visible,
      props.containerHeight,
      onPause,
      isPaused,
    ],
  );
}

const styles = StyleSheet.create({
  mainComponent: {
    padding: 8,
    marginHorizontal: 2,
    borderWidth: StyleSheet.hairlineWidth,
  },
  mainScrollView: { height: 296 },
  quickbarScrollView: { height: 226, overflow: "hidden" },
  tabsContainer: {
    marginHorizontal: 8,
    marginBottom: 8,
    flexDirection: "row",
    alignItems: "flex-end",
    justifyContent: "space-evenly",
  },
  tab: {
    borderWidth: StyleSheet.hairlineWidth,
    borderRadius: 8,
    padding: 8,
    top: -1,
    cursor: "pointer",
  },
  selectedTab: {
    borderTopLeftRadius: 0,
    borderTopRightRadius: 0,
    borderTopWidth: 0,
    paddingTop: 16,
    position: "relative",
  },
  accessibilityHidden: {
    width: 1,
    height: 1,
    position: "absolute",
    left: 0,
    top: 0,
  },
});
