import { t } from "../../../../utils/I18n";
import { jumpEventToMessageIfExists } from "../../../events/Events";
import { GameState } from "../../../GameState";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { StoneskinTemporaryEffect } from "../../../spells/earth/Stoneskin";
import { StrengthTemporaryEffect } from "../../../spells/fire/Strength";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import {
  calculateAttackTarget,
  getCurrentEnemyHPFraction,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class GoblinChief extends Enemy {
  getId(): string {
    return "goblinChief";
  }

  getName(): string {
    return t("Goblin Chief");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 5000,
      attack: 55,
      defense: 50,
      attackDelay: 2.7,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./goblinChief.png");
  }

  isBoss(): boolean {
    return true;
  }

  getLevel(): number {
    return 18;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 10000;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 100;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (getCurrentEnemyHPFraction(state) < 0.5) {
      if (
        !hasTemporaryEffect(
          state,
          StoneskinTemporaryEffect.getId(),
          AttackTarget.Enemy,
        )
      ) {
        return {
          name: t("Goblin Stoneskin"),
          delay: this.getAttackDelay(state),
          transform: grantTemporaryEffect(
            StoneskinTemporaryEffect.getId(),
            180,
            {
              target: AttackTarget.Enemy,
              params: { commonBuff: { DefenseMultiplier: 1.25 } },
            },
          ),
        };
      }
      if (
        !hasTemporaryEffect(
          state,
          StrengthTemporaryEffect.getId(),
          AttackTarget.Enemy,
        )
      ) {
        return {
          name: t("Goblin Strength"),
          delay: this.getAttackDelay(state),
          transform: grantTemporaryEffect(
            StrengthTemporaryEffect.getId(),
            180,
            {
              target: AttackTarget.Enemy,
              params: { commonBuff: { AttackMultiplier: 1.2 } },
            },
          ),
        };
      }
    }
    if (
      getResourceAmount(state, Resource.Coins) >= 10000 &&
      Math.random() < 0.2
    ) {
      return {
        name: t("Steal Lots of Coins!"),
        delay: this.getAttackDelay(state),
        transform: (state) =>
          grantResource(Resource.Coins, -(1000 + 4000 * Math.random()))(state),
      };
    }
    if (Math.random() < 0.4) {
      return {
        name: t("Strong Attack"),
        delay: this.getAttackDelay(state) * 2,
        transform: (state) =>
          standardEnemyAttackEffect(state, this.getAttack(state) * 2),
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = jumpEventToMessageIfExists(state, "goblinDen", "afterFight");
    return state;
  }

  getTips(): string {
    return t(
      "This Goblin is much larger than the others we have gotten used to fighting. It is really quite strong, with high :attack: and :defense:, and it will become even stronger once it is wounded enough. What's worse is, it will steal a ton of :coins: from us - by the thousands! We should probably not fight it if we are not ready to lose our stockpile. On the other hand, if it just ends up stealing our :coins:, that is one less attack we need to endure... right?",
    );
  }
}

export default new GoblinChief();
