import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { EquippableItem } from "../equipment/EquippableItem";
import { isItemQualityUnlocked } from "../Items";
import { TransmutationSpell } from "./TransmutationSpell";

export abstract class EquipmentTransmutationSpell extends TransmutationSpell {
  abstract getItem(): EquippableItem;

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.ExplorationUnlocked) && super.isVisible(state);
  }

  timesCraftable(state: GameState): number {
    return isItemQualityUnlocked(state)
      ? Math.min(9999, super.timesCraftable(state))
      : 1;
  }

  simplifyCrafting(): boolean {
    return false;
  }
}
