import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { standardPlayerAttackEffect } from "../exploration/Exploration";
import { GameState } from "../GameState";
import {
  getTemporaryEffectEndTime,
  hasTemporaryEffect,
} from "../temporaryeffects/TemporaryEffects";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { Spell } from "./Spell";

export function getStandardRangeEffectText(
  average: number,
  variance: number,
  explanation?: string,
): string {
  const min = average - variance,
    max = average + variance;
  if (variance == 0) {
    if (!explanation) {
      return formatNumber(min);
    }
    return t("^{{effect}}^<{{explanation}}>", {
      effect: formatNumber(min),
      explanation,
    });
  }
  if (!explanation) {
    return t("{{min}}~{{max}}", {
      min: formatNumber(min),
      max: formatNumber(max),
    });
  }
  return t("^{{min}}~{{max}}^<{{explanation}}>", {
    min: formatNumber(min),
    max: formatNumber(max),
    explanation,
  });
}

export function getStandardAttackEffectText(
  average: number,
  variance: number,
  explanation?: string,
): string {
  return t("{{effect}}:attack:", {
    effect: getStandardRangeEffectText(average, variance, explanation),
  });
}

export function getStandardSpellAttackEffect(
  state: GameState,
  spell: Spell,
  extras?: object,
): GameState {
  const effects = spell.getActionEffects(state);
  state = standardPlayerAttackEffect(
    state,
    effects.average + (Math.random() - 0.5) * effects.variance * 2,
    {
      cause: spell.getSpellName(),
      tags: spell.getTags(),
      ...(extras ?? {}),
    },
  );
  return state;
}

export function getStandardSpellAttackEffectText(
  state: GameState,
  spell: Spell,
): string {
  const effects = spell.getActionEffects(state);
  const explanation = spell.getActionEffectExplanations(
    state,
    "average",
  ).average;
  return getStandardAttackEffectText(
    effects.average,
    effects.variance,
    explanation,
  );
}

export function isCastingRecommendedTemporaryEffect(
  state: GameState,
  effectName: string,
  target?: AttackTarget,
): boolean {
  if (hasTemporaryEffect(state, effectName, target)) {
    return (
      getTemporaryEffectEndTime(state, effectName, target) -
        getSecondsPlayed(state) <
      2.0
    );
  }
  return true;
}
