import memoize from "sonic-memoize";
import { GameState, GameStateTransform } from "./GameState";
import { DataStore } from "./generic/DataStore";

export function hasFlag(state: GameState, flag: string) {
  const cached = state.temp.flagCache.get(flag);
  if (cached === undefined) {
    state.temp.flagCache.set(flag, state.run.flags?.[flag] || false);
    return state.temp.flagCache.get(flag);
  }
  return cached;
}

export const SetFlagListeners = new DataStore<
  (state: GameState, flag: string) => GameState
>();

function setFlagImpl(flag: string, state: GameState) {
  if (!state.run.flags?.[flag]) {
    state.temp.flagCache.delete(flag);
  }
  const oldValue = state.run.flags[flag] ?? false;
  state.run.flags[flag] = true;
  if (!oldValue) {
    SetFlagListeners.getAll().forEach((transform) => {
      state = transform(state, flag);
    });
  }
  if (!state.world.flags?.["ever_" + flag]) {
    state.temp.worldFlagCache.delete("ever_" + flag);
  }
  state.world.flags["ever_" + flag] = true;
  return state;
}

export function hasEverHadFlag(state: GameState, flag: string) {
  return hasWorldFlag(state, "ever_" + flag);
}

export const setFlag = memoize((flag: string) => setFlagImpl.bind(this, flag));

function clearFlagImpl(flag: string, state: GameState) {
  state.run.flags[flag] = false;
  state.temp.flagCache.delete(flag);
  return state;
}

export const clearFlag = memoize((flag: string) =>
  clearFlagImpl.bind(this, flag),
);

export function setWorldFlagForNextRunImpl(
  flag: string,
  state: GameState,
): GameState {
  state.run.newFlagsOnRetirement[flag] = true;
  return state;
}

export const setWorldFlagForNextRun = memoize((flag: string) =>
  setWorldFlagForNextRunImpl.bind(this, flag),
);

export function setWorldFlagImpl(flag: string, state: GameState): GameState {
  if (!state.world.flags?.[flag]) {
    state.temp.worldFlagCache.delete(flag);
  }
  state.world.flags[flag] = true;
  return state;
}

export const setWorldFlag = memoize((flag: string) =>
  setWorldFlagImpl.bind(this, flag),
);

function clearWorldFlagImpl(flag: string, state: GameState) {
  state.world.flags[flag] = false;
  state.temp.worldFlagCache.delete(flag);
  return state;
}

export function clearWorldFlag(this: any, flag: string): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return clearWorldFlagImpl.bind(this, flag);
}

export function hasWorldFlag(state: GameState, flag: string): boolean {
  const cached = state.temp.worldFlagCache.get(flag);
  if (cached === undefined) {
    state.temp.worldFlagCache.set(flag, state.world.flags?.[flag] || false);
    return state.temp.worldFlagCache.get(flag);
  }
  return cached;
}

export function hasWorldFlagIncludingNextRun(
  state: GameState,
  flag: string,
): boolean {
  return (
    state?.world?.flags?.[flag] ||
    state?.run?.newFlagsOnRetirement?.[flag] ||
    false
  );
}

function setCombatVariableImpl(variable: string, value: any, state: GameState) {
  state.run.exploration.variables[variable] = value;
  return state;
}

export function setCombatVariable(
  this: any,
  variable: string,
  value: any,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return setCombatVariableImpl.bind(this, variable, value);
}

function clearCombatVariableImpl(variable: string, state: GameState) {
  delete state.run.exploration.variables[variable];
  return state;
}

export function clearCombatVariable(
  this: any,
  variable: string,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return clearCombatVariableImpl.bind(this, variable);
}

export function resetAllCombatVariables(state: GameState): GameState {
  state.run.exploration.variables = {};
  return state;
}

export function getCombatVariable(state: GameState, variable: string): any {
  return state.run.exploration?.variables[variable];
}
