import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import VenomPowder from "../../../items/material/VenomPowder";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { poison } from "../../../temporaryeffects/Poison";
import { shrunken } from "../../../temporaryeffects/Shrunken";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class PoisonousToad extends Enemy {
  getId(): string {
    return "poisonousToad";
  }

  getName(): string {
    return t("Poisonous Toad");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 450,
      attack: 34,
      defense: 12,
      attackDelay: 1.8,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./poisonousToad.png");
  }

  getLevel(): number {
    return 9;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 50;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 16;
  }

  getNextAction(state: GameState): BattlerAction {
    if (
      !hasTemporaryEffect(state, poison.getId(), AttackTarget.Player) &&
      Math.random() < 0.2
    ) {
      return {
        name: t("Poisonous Spit"),
        delay: this.getAttackDelay(state),
        transform: (state) =>
          grantTemporaryEffect(poison.getId(), 15, {
            target: AttackTarget.Player,
            params: {
              magnitude: this.getAttack(state) / 12,
            },
          })(state),
      };
    }
    if (
      !hasTemporaryEffect(state, shrunken.getId(), AttackTarget.Enemy) &&
      Math.random() < 0.1
    ) {
      return {
        name: t("Shrink!"),
        delay: this.getAttackDelay(state) * 1.15,
        transform: (state) =>
          grantTemporaryEffect(shrunken.getId(), 15, {
            target: AttackTarget.Enemy,
            params: {
              commonBuff: { Dodge: 100 },
            },
          })(state),
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 1,
        chance: 0.25,
      },
      {
        itemId: VenomPowder.getId(),
        amount: 2,
        chance: 0.15,
      },
    ];
  }
}

export default new PoisonousToad();
