import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import {
  ActionArea,
  ActionSubcategories,
  getSubcategoriesByArea,
  translateActionSubcategory,
} from "../../backend/action/ActionAreas";
import { getVisibleActionsForAreaBySubcategory } from "../../backend/action/Actions";
import { isExplorationUnlocked } from "../../backend/exploration/Exploration";
import { idOf } from "../../backend/generic/Identifiable";
import { ActionContainer } from "./ActionContainer";
import CategoryContainer from "./CategoryContainer";
import { useGameState } from "./useGameState";

type ActionsContainerProps = {
  area: ActionArea;
};

function ActionSubcategoryContainer(
  props: ActionsContainerProps & {
    subcategory: ActionSubcategories;
  },
) {
  const state = useGameState();
  const actionsBySubcategory = getVisibleActionsForAreaBySubcategory(
    state,
    props.area,
  );
  const subcategory = props.subcategory;
  const actions = actionsBySubcategory[subcategory] ?? [];
  const actionNamesStr = actions.map(idOf).join(",");

  const actionContainers = useMemo(() => {
    return actions.map((action) => {
      return <ActionContainer key={action.getId()} action={action} />;
    });
  }, [actionNamesStr]);

  return useMemo(() => {
    if (actions.length === 0) {
      return null;
    }
    return (
      <CategoryContainer
        title={translateActionSubcategory(subcategory)}
        key={subcategory}
        initiallyExpanded={
          subcategory != ActionSubcategories.CHANNELING ||
          !isExplorationUnlocked(state)
        }
      >
        <View style={styles.underCategoryPadding} />
        <View key={actions[0].getId()}>{actionContainers[0]}</View>
        {actionContainers.slice(1).map((actionContainer, idx) => {
          return <View key={actions[idx + 1].getId()}>{actionContainer}</View>;
        })}
      </CategoryContainer>
    );
  }, [actionContainers, subcategory]);
}

export default function ActionsContainer(props: ActionsContainerProps) {
  const actionCategories = getSubcategoriesByArea(props.area).map(
    (subcategory) => (
      <ActionSubcategoryContainer
        {...props}
        subcategory={subcategory}
        key={subcategory}
      />
    ),
  );

  return <View>{actionCategories}</View>;
}

const styles = StyleSheet.create({
  underCategoryPadding: {
    height: 8,
  },
});
