import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { clearCombatStatCache, CombatStat } from "../exploration/CombatStats";
import { getCurrentHPFromTarget } from "../exploration/Exploration";
import { GameState } from "../GameState";
import { registerTimeTickListener } from "../timetick/TimeTick";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Disease extends TemporaryEffect {
  getId(): string {
    return "disease";
  }
  getDisplayName(): string {
    return t("Disease");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Lowers Max HP to the current HP");
  }
}

export const disease = new Disease();

export function loadDisease() {
  TemporaryEffects.register(disease);

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[target, CombatStat.MaxHP]],
      target + "disease",
      disease.getDisplayName(),
      TransformationType.Override,
      (state) => {
        if (hasTemporaryEffect(state, disease.getId(), target)) {
          return Math.max(getCurrentHPFromTarget(state, target), 1);
        }
        return null;
      },
    );
    registerTimeTickListener(
      "diseaseCacheClearerHP" + target,
      (state, timeElapsedSec) => {
        if (hasTemporaryEffect(state, disease.getId(), target)) {
          return clearCombatStatCache(state, target, CombatStat.MaxHP);
        }
        return state;
      },
    );
  }
  registerTemporaryEffectCombatStatCacheClearer(
    disease.getId(),
    CombatStat.MaxHP,
  );
}
