import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { getUnlockedElements, SpellElement } from "../../spells/Elements";
import { Enhancement } from "../Enhancement";
import { monstiumCollection } from "../exploration/MonstiumCollection";
import { superheatedCatalysts } from "../production/SuperheatedCatalysts";

class MonstiumCatalysts extends Enhancement {
  getId(): string {
    return "monstiumCatalysts";
  }
  getBaseName(): string {
    return t("Monstium Catalysts");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_CATALYSTS;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 2000,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 12,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [monstiumCollection, superheatedCatalysts];
  }
  isVisible(state: GameState): boolean {
    return (
      super.isVisible(state) &&
      getUnlockedElements(state).includes(SpellElement.Poison)
    );
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Discover a new type of furniture, which is able to increase the amount of Monstium obtained from enemies.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t('Unlock furniture "Monstium Catalyst"');
  }
}

export const monstiumCatalysts = new MonstiumCatalysts();
