import { fromEntries, keysOf, valuesOfCached } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { ActionArea, ActionSubcategory } from "../action/ActionAreas";
import { registerAction } from "../action/Actions";
import { cacheIndefinitely, clearCacheKey } from "../Caching";
import { createHypotheticalState, GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { DataStore } from "../generic/DataStore";
import { Resource } from "../Resources";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  clearTransformationCache,
  registerTransformation,
  TransformationTag,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import {
  getElementLevel,
  getTotalExpRequiredForLevel,
  getUnlockedElements,
  SpellElement,
} from "./Elements";
import { Spell } from "./Spell";
import { clearVisibleSpellsCache } from "./VisibleSpellsCache";

const Spells = new DataStore<Spell>();

export function registerSpell(spell: Spell) {
  Spells.register(spell);
  registerAction(spell);
}

export { Spells };

export function getAllSpellsByAscendingLevel(): Spell[] {
  return Spells.getAll().sort(
    (a, b) =>
      Math.max(...valuesOfCached(a.getLevelRequirements())) -
      Math.max(...valuesOfCached(b.getLevelRequirements())),
  );
}

export function getVisibleSpellsForArea(
  state: GameState,
  area: ActionArea,
): Partial<Record<ActionSubcategory, Spell[]>> {
  const spellsBySubcategory: Partial<Record<ActionSubcategory, Spell[]>> = {};
  for (let spell of Spells.getAll()) {
    if (
      (area == null || spell.getAreas()?.[area] != null) &&
      spell.isVisible(state)
    ) {
      for (let subcategory of spell.getAreas()[area] ?? []) {
        if (spellsBySubcategory?.[subcategory] === undefined) {
          spellsBySubcategory[subcategory] = [];
        }
        spellsBySubcategory[subcategory]?.push(spell);
      }
    }
  }
  return spellsBySubcategory;
}

export function getVisibleSpellsByElement(
  state: GameState,
  area?: ActionArea,
): Record<SpellElement, Spell[]> {
  return cacheIndefinitely(
    state,
    (_state) => "spellByElement:" + (area ?? "undefined"),
    (state) => {
      const spellsByElement: Record<SpellElement, Spell[]> = fromEntries(
        valuesOfCached(SpellElement).map((element) => [element, [] as Spell[]]),
      );
      const unlockedElements = getUnlockedElements(state);
      for (let spell of Spells.getAll()) {
        if (
          (area == null || spell.getAreas()?.[area] != null) &&
          spell.isVisible(state)
        ) {
          const elementRequirements = spell.getLevelRequirements();
          for (let element in elementRequirements) {
            if (!unlockedElements.includes(element as SpellElement)) {
              continue;
            }
            if (spellsByElement?.[element] === undefined) {
              spellsByElement[element] = [];
            }
            spellsByElement[element].push(spell);
          }
        }
      }
      for (let element of keysOf(spellsByElement)) {
        if (spellsByElement[element].length === 0) {
          delete spellsByElement[element];
        } else {
          spellsByElement[element].sort(
            (a, b) =>
              (b.getLevelRequirements()[element] || 0) -
              (a.getLevelRequirements()[element] || 0),
          );
        }
      }
      return spellsByElement;
    },
  );
}

export function getVisibleSpellsForElement(
  state: GameState,
  element: SpellElement,
  area?: ActionArea,
  extraLevels?: number,
) {
  if (!extraLevels) {
    return getVisibleSpellsByElement(state, area)?.[element] || [];
  }
  const fakeState = createHypotheticalState(state);
  fakeState.run.elementExperience[element] = getTotalExpRequiredForLevel(
    fakeState,
    getElementLevel(state, element) + extraLevels,
    element,
  );
  return getVisibleSpellsByElement(fakeState, area)?.[element] || [];
}

export function getSpellsForElementThatCanBeLearnedSoon(
  state: GameState,
  element: SpellElement,
) {
  const currentSpells = getVisibleSpellsForElement(state, element);
  const otherUnlockedElements = getUnlockedElements(state).filter(
    (unlockedElement) => unlockedElement != element,
  );

  let learnedSoon: Spell[] = [];

  for (let elementIdx in otherUnlockedElements) {
    const otherElement = otherUnlockedElements[elementIdx];
    const fakeState = createHypotheticalState(state);
    fakeState.run.elementExperience[otherElement] = getTotalExpRequiredForLevel(
      fakeState,
      getElementLevel(state, otherElement) + 1,
      otherElement,
    );
    const soonVisibleSpells = getVisibleSpellsForElement(fakeState, element);
    learnedSoon = learnedSoon.concat(
      soonVisibleSpells.filter((spell) => !currentSpells.includes(spell)),
    );
  }

  return learnedSoon;
}

export function getRecentSpellsCast(state: GameState): Spell[] {
  return (state.run.recentSpellsCast || [])
    .map(Spells.getById)
    .filter((spell) => spell != null);
}

export function addRecentSpellCast(spell: Spell, state: GameState) {
  const id = spell.getId();
  state.run.recentSpellsCast = state.run.recentSpellsCast.filter(
    (recentSpellName) => recentSpellName != id,
  );
  state.run.recentSpellsCast.unshift(id);
  state.run.recentSpellsCast = state.run.recentSpellsCast.slice(0, 3);
  return state;
}

export function setLastCastedTime(spell: Spell, state: GameState) {
  state.run.lastSpellCastTimes[spell.getId()] = getSecondsPlayed(state);
  return state;
}

export function getFavoriteSpells(state: GameState): Spell[] {
  return state.run?.favoriteSpells?.map((id) => Spells.getById(id)) ?? [];
}

export function addFavoriteSpell(state: GameState, spell: Spell): GameState {
  if (isInFavoriteSpells(state, spell)) {
    return state;
  }
  state.run.favoriteSpells.push(spell.getId());
  return state;
}

export function removeFromFavoriteSpells(
  state: GameState,
  spell: Spell,
): GameState {
  const index = state.run.favoriteSpells.findIndex(
    (id) => spell.getId() === id,
  );
  if (index < 0) {
    return state;
  }
  state.run.favoriteSpells.splice(index, 1);
  return state;
}

export function isInFavoriteSpells(state: GameState, spell: Spell): boolean {
  return state.run.favoriteSpells.findIndex((id) => spell.getId() === id) >= 0;
}

export function toggleEmpowered(state: GameState, spell: Spell): GameState {
  const empowered = spell.isEmpowered(state);
  state.run.empoweredSpells[spell.getId()] = !empowered;
  state.temp.spellManaCostCache = {};
  state.temp.spellExpCache = {};
  state = callSpellCacheListeners(state);
  return state;
}

const spellCacheListeners = [];

function callSpellCacheListeners(state: GameState): GameState {
  for (let listener of spellCacheListeners) {
    state = listener(state);
  }
  return state;
}

export function registerSpellCacheClearedListener(
  fn: (state: GameState) => GameState,
) {
  spellCacheListeners.push(fn);
}

export function clearAutocastPointsCache(state: GameState): GameState {
  state.temp.autocastPointsRequirementCache = {};
  return callSpellCacheListeners(state);
}

export function registerAutocastPointsClearer(
  id: string,
  valueFn: (state: GameState) => any,
) {
  addGameStateListener(
    id + "-autocastPointsClearer",
    (state) => {
      return clearAutocastPointsCache;
    },
    valueFn,
  );
}

export function clearManaCostCache(state: GameState): GameState {
  state.temp.spellManaCostCache = {};
  state.temp.spellExpCache = {};
  return callSpellCacheListeners(state);
}

export function registerManaCostClearer(
  id: string,
  valueFn: (state: GameState) => any,
) {
  addGameStateListener(
    id + "-ManaCostClearer",
    (state) => {
      return clearManaCostCache;
    },
    valueFn,
  );
}

export function clearCanCastSpellCache(state: GameState): GameState {
  state = clearTransformationCache(
    new Set([TransformationTags.CanCastSpell]),
    state,
  );
  return callSpellCacheListeners(state);
}

export function registerCanCastSpellClearer(
  id: string,
  valueFn: (state: GameState) => any,
) {
  addGameStateListener(
    id + "-CanCastSpellClearer",
    (state) => {
      return clearCanCastSpellCache;
    },
    valueFn,
  );
}

export function clearSpellCooldownCache(state: GameState): GameState {
  state = clearTransformationCache(
    new Set([TransformationTags.SpellCooldown]),
    state,
  );
  return callSpellCacheListeners(state);
}

export function registerSpellCooldownClearer(
  id: string,
  valueFn: (state: GameState) => any,
) {
  addGameStateListener(
    id + "-spellCooldownCapClearer",
    (state) => clearSpellCooldownCache,
    valueFn,
  );
}

export function clearSpellDurationCache(state: GameState): GameState {
  for (let key of state.temp.cache.keys()) {
    if (key.startsWith("minAutocastDelayDuration")) {
      state = clearCacheKey(state, () => key);
    }
  }
  return callSpellCacheListeners(state);
}

export function clearAllSpellCaches(state: GameState): GameState {
  state = clearSpellCooldownCache(state);
  state = clearCanCastSpellCache(state);
  state = clearManaCostCache(state);
  state = clearAutocastPointsCache(state);
  state = clearVisibleSpellsCache(state);
  state = clearSpellDurationCache(state);
  return state;
}

export function registerEmpowerEffects(
  spell: Spell,
  tags: TransformationTag[],
  multiplier: number,
) {
  registerTransformation(
    [[spell.getId(), ...tags]],
    spell.getId() + "empowered" + tags.join(),
    t("Spell Empowering"),
    TransformationType.Multiplier,
    (state) => (spell.isEmpowered(state) ? multiplier : 1),
  );
}

export function registerStandardEmpowerEffects(
  spell: Spell,
  actionEffectMultiplier: number,
  manaCostMultiplier: number,
) {
  registerEmpowerEffects(
    spell,
    [TransformationTags.ActionEffect],
    actionEffectMultiplier,
  );
  registerEmpowerEffects(
    spell,
    [TransformationTags.Cost, Resource.Mana],
    manaCostMultiplier,
  );
}

export function registerBuffEmpowerEffects(
  spell: Spell,
  effectMagnitudeMultiplier: number,
  manaCostMultiplier: number,
) {
  registerEmpowerEffects(
    spell,
    [TransformationTags.TemporaryEffectMagnitude],
    effectMagnitudeMultiplier,
  );
  registerEmpowerEffects(
    spell,
    [TransformationTags.Cost, Resource.Mana],
    manaCostMultiplier,
  );
}
