import { valuesOf } from "../../utils/CoreUtils";
import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { SpellElement, translateElement } from "../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectData,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class ResistElementBase extends TemporaryEffect {
  constructor(element: SpellElement) {
    super();
    this.element = element;
  }
  element: SpellElement;
  getId(): string {
    return "elementalProtection" + this.element;
  }
  getDisplayName(): string {
    return t("Resist {{element}}", { element: translateElement(this.element) });
  }
  isBeneficial(): boolean {
    return true;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("{{amount}} {{element}} damage received", {
      amount: formatMultiplier(temporaryEffectData.params.damageMultiplier),
      element: translateElement(this.element),
    });
  }
}

export function resistElementTemporaryEffect(
  element: SpellElement,
): ResistElementBase {
  return TemporaryEffects.getById(
    "elementalProtection" + element,
  ) as ResistElementBase;
}

export function loadElementalProtection() {
  for (let element of valuesOf(SpellElement)) {
    TemporaryEffects.register(new ResistElementBase(element));
    registerTransformation(
      [[TransformationTags.AttackDamageReceived]],
      "elementalProtection" + element,
      t("Resist {{element}}", { element: translateElement(element) }),
      TransformationType.Multiplier,
      (state, params) => {
        return hasTemporaryEffect(
          state,
          "elementalProtection" + element,
          params.target,
        ) && (params.tags ?? []).includes(element)
          ? getTemporaryEffectData(
              state,
              "elementalProtection" + element,
              params.target,
            ).params.damageMultiplier
          : 1.0;
      },
    );
  }
  registerTransformation(
    [
      [TransformationTags.PoisonDoTAmount],
      [TransformationTags.CorrosionDoTAmount],
    ],
    "poisonResistanceFromElementalProtection",
    t("Resist Poison"),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(
        state,
        "elementalProtectionPoison",
        params.target,
      )
        ? getTemporaryEffectData(
            state,
            "elementalProtectionPoison",
            params.target,
          ).params.damageMultiplier
        : 1.0;
    },
  );
}
