import { memoizeWithLimit } from "sonic-memoize";
import { formatMultiplier, formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import {
  cacheByArbitraryVersion,
  cacheByTickCount,
  cacheIndefinitely,
} from "../Caching";
import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { Resource } from "../Resources";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { SpellElement } from "../spells/Elements";
import {
  TransformationTags,
  TransformationValueType,
} from "./TransformationTags";

export enum TransformationType {
  Addition = "Addition",
  Multiplier = "Multiplier",
  Power = "Power",
  Override = "Override",
}

export type TransformationTag =
  | TransformationTags
  | Resource
  | SpellElement
  | CombatStat
  | AttackTarget
  | string;

export type Transformation = (
  state: GameState,
  params: Record<string, any>,
  previousValue: number,
) => number | undefined;

export type TransformationData = {
  id: string;
  description: string;
  transformation: Transformation;
  type: TransformationType;
  tags: TransformationTag[][];
  lastValue: number | undefined;
};

const transformationDatabase: Partial<
  Record<TransformationTag, TransformationData[]>
> = {};

const allTransformations: Record<string, TransformationData> = {};

export function registerTransformation(
  tags: TransformationTag[][],
  transformationId: string,
  transformationDescription: string,
  transformationType: TransformationType,
  transformation: Transformation,
) {
  const data = {
    id: transformationId,
    transformation,
    description: transformationDescription,
    type: transformationType,
    tags,
    lastValue: undefined,
  };

  const allTags = new Set(tags.flat(2));
  allTags.forEach((tag) => {
    const forTag = transformationDatabase?.[tag] ?? [];
    if (!forTag.some((preExisting) => preExisting.id === transformationId)) {
      forTag.push(data);
      transformationDatabase[tag] = forTag;
    }
  });
  allTransformations[transformationId] = data;
  const recalculateEveryTick = false; //allTags.has(TransformationTags.PerBuildingCap);

  if (recalculateEveryTick) {
    addGameStateListener("t:cache:" + transformationId, (state) => {
      const value = transformation(state, {}, 10);
      if (value != allTransformations[transformationId].lastValue) {
        allTransformations[transformationId].lastValue = value;
        return clearTransformationCache.bind(null, allTags);
      }
    });
  }
}

const cacheMisses: Map<string, number> = new Map();

const getTransformationsThatApply = memoizeWithLimit(
  (tags: TransformationTag[], prejoinedTags?: string) => {
    // const key = joinedTags ?? tags.join("");
    // const oldValue = transformationsThatApplyCache.get(key);
    // if (oldValue == null) {
    //   const allTransformations = tags
    //     .map((tag) => transformationDatabase?.[tag] ?? [])
    //     .flat();
    //   const allTransformationsThatApply = allTransformations.filter((data) =>
    //     data.tags.some((targetTags) =>
    //       targetTags.every((targetTag) => tags.includes(targetTag)),
    //     ),
    //   );
    //   const uniqueTransformations = [...new Set(allTransformationsThatApply)];
    //   const result = Object.fromEntries(
    //     Object.values(TransformationType).map((type) => [
    //       type,
    //       uniqueTransformations.filter((data) => data.type === type),
    //     ]),
    //   ) as Record<TransformationType, TransformationData[]>;
    //   transformationsThatApplyCache.set(key, result);
    //   return result;
    // }
    // return oldValue;

    const joinedTags = prejoinedTags ?? tags.join();
    cacheMisses.set(joinedTags, (cacheMisses.get(joinedTags) ?? 0) + 1);
    const allTransformations = tags
      .map((tag) => transformationDatabase?.[tag] ?? [])
      .flat();
    const allTransformationsThatApply = allTransformations.filter((data) =>
      data.tags.some((targetTags) =>
        targetTags.every((targetTag) => tags.includes(targetTag)),
      ),
    );
    const uniqueTransformations = [...new Set(allTransformationsThatApply)];
    const result = Object.fromEntries(
      Object.values(TransformationType).map((type) => [
        type,
        uniqueTransformations.filter((data) => data.type === type),
      ]),
    ) as Record<TransformationType, TransformationData[]>;
    return result;
  },
  10000,
);

// Generally use applyTransformations. However, this one can be used for example
// to display modified effects when multipliers affect the final effect.
// This will prove inaccurate if, for example, there are overrides or similar things.
export function applyMultiplierTransformations(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
) {
  const transformParams = params ?? {};
  transformParams.tags = tags;

  const transformations = getTransformationsThatApply(tags);

  // Apply multiplication transforms only
  const multiplicationTransformations =
    transformations[TransformationType.Multiplier];
  const afterMultiplication = multiplicationTransformations
    .map((data) =>
      data.transformation(state, transformParams, startingValue ?? 0),
    )
    .reduce((prev, current) => (prev ?? 1.0) * (current ?? 1.0), startingValue);

  return afterMultiplication;
}

export function applyTransformations(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
  joinedTags?: string,
) {
  const transformParams = params ?? {};
  const oldTransformTags = transformParams?.tags;
  transformParams.tags =
    transformParams?.tags != null ? transformParams.tags.concat(tags) : tags;

  const transformations = getTransformationsThatApply(tags, joinedTags);

  // Step 1: apply addition transforms
  const additionTransformations = transformations[TransformationType.Addition];
  let afterAddition = startingValue;
  for (let transformation of additionTransformations) {
    afterAddition +=
      transformation.transformation(state, transformParams, startingValue) ?? 0;
  }

  // Step 2: apply power transforms (rare)
  const powerTransformations = transformations[TransformationType.Power];
  let afterPower = afterAddition;
  for (let transformation of powerTransformations) {
    afterPower = Math.pow(
      afterPower,
      transformation.transformation(state, transformParams, afterAddition) ??
        1.0,
    );
  }

  // Step 3: apply multiplication transforms
  const multiplicationTransformations =
    transformations[TransformationType.Multiplier];
  let afterMultiplication = afterPower;
  for (let transformation of multiplicationTransformations) {
    afterMultiplication *=
      transformation.transformation(state, transformParams, afterPower) ?? 1.0;
  }

  // Step 4: finally, apply override transforms
  const overrideTransformations = transformations[TransformationType.Override];
  let afterOverride = afterMultiplication;
  for (let transformation of overrideTransformations) {
    const newOverride = transformation.transformation(
      state,
      transformParams,
      afterMultiplication,
    );
    if (newOverride != null) {
      afterOverride = newOverride;
    }
  }

  transformParams.tags = oldTransformTags;

  return afterOverride as number;
}

const missMap = {};
let missCount = 0;

const getCacheKeyStuff = memoizeWithLimit(
  (
    tags: TransformationTag[],
    startingValue: number,
    params?: Record<string, any>,
  ) => {
    const joinedTags = tags.join("");
    missMap[joinedTags] = (missMap[joinedTags] ?? 0) + 1;
    missCount++;
    return [
      joinedTags,
      "t:" + joinedTags + startingValue + JSON.stringify(params),
    ];
  },
  10000,
);

export function applyTransformationsCached(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
): number {
  const cacheKey = getCacheKeyStuff(tags, startingValue, params);
  return cacheByTickCount(state, cacheKey[1], (state) =>
    applyTransformations(tags, state, startingValue, params, cacheKey[0]),
  );
}

export function applyTransformationsPermaCached(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
): number {
  const cacheKey = getCacheKeyStuff(tags, startingValue, params);
  return cacheIndefinitely(
    state,
    (_state) => cacheKey[1],
    (state) =>
      applyTransformations(tags, state, startingValue, params, cacheKey[0]),
  );
}

export function clearTransformationCache(
  tags: Set<TransformationTag>,
  state: GameState,
): GameState {
  const cache = state.temp.cache;
  for (let key of cache.keys()) {
    let deleteKey = true;
    for (let tag of tags) {
      if (!key.includes(tag)) {
        deleteKey = false;
        break;
      }
    }

    if (deleteKey) {
      cache.delete(key);
    }
  }
  return state;
}

export function explainTransformations(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
): Record<
  TransformationType,
  Array<{ id: string; description: string; value: number | undefined }>
> {
  const transformParams = params ?? {};
  const oldTransformTags = transformParams?.tags;
  transformParams.tags =
    transformParams?.tags != null ? transformParams.tags.concat(tags) : tags;
  const output: Record<
    TransformationType,
    Array<{ id: string; description: string; value: number | undefined }>
  > = {
    Power: [],
    Addition: [],
    Multiplier: [],
    Override: [],
  };

  // Step 1: get the transformations that apply
  const allTransformationsThatApply = getTransformationsThatApply(tags);

  // Step 2: apply addition transforms
  const additionTransformations = allTransformationsThatApply.Addition;
  const afterAddition = additionTransformations
    .map((data) => {
      const value = data.transformation(
        state,
        transformParams,
        startingValue ?? 0,
      );
      output.Addition.push({
        id: data.id,
        description: data.description,
        value,
      });
      return value;
    })
    .reduce((prev, current) => (prev ?? 0) + (current ?? 0), startingValue);

  // Step 3: apply power transforms (rare)
  const powerTransformations = allTransformationsThatApply.Power;
  const afterPower = powerTransformations
    .map((data) => {
      const value = data.transformation(
        state,
        transformParams,
        afterAddition ?? 0,
      );
      output.Power.push({ id: data.id, description: data.description, value });
      return value;
    })
    .reduce(
      (prev, current) => Math.pow(prev ?? 1.0, current ?? 1.0),
      afterAddition,
    );

  // Step 4: apply multiplication transforms
  const multiplicationTransformations = allTransformationsThatApply.Multiplier;
  const afterMultiplication = multiplicationTransformations
    .map((data) => {
      const value = data.transformation(
        state,
        transformParams,
        afterPower ?? 0,
      );
      output.Multiplier.push({
        id: data.id,
        description: data.description,
        value,
      });
      return value;
    })
    .reduce((prev, current) => (prev ?? 1.0) * (current ?? 1.0), afterPower);

  // Step 5: finally, apply override transforms
  const overrideTransformations = allTransformationsThatApply.Override;
  const _afterOverride = overrideTransformations
    .map((data) => {
      const value = data.transformation(
        state,
        transformParams,
        afterMultiplication ?? 0,
      );
      output.Override.push({
        id: data.id,
        description: data.description,
        value,
      });
      return value;
    })
    .reduce((prev, current) => current ?? prev, afterMultiplication);

  transformParams.tags = oldTransformTags;

  return output;
}

export function explainTransformationsText(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
): string {
  const cacheKey = getCacheKeyStuff(tags, startingValue, params);
  return cacheByArbitraryVersion(
    state,
    "explainCache:" + cacheKey,
    (state) =>
      explainTransformationsTextUncached(tags, state, startingValue, params),
    (state) => applyTransformations(tags, state, startingValue, params),
  );
}

export function explainTransformationsTextUncached(
  tags: TransformationTag[],
  state: GameState,
  startingValue: number,
  params?: Record<string, any>,
): string {
  const explained = explainTransformations(tags, state, startingValue, params);
  const unit = params?.unit ?? "";
  const type = params?.valueType ?? TransformationValueType.Basic;

  const overrides = explained.Override.filter((value) => value.value != null);

  if (overrides.length > 0) {
    return t("Overridden: {{amount}}{{unit}} ({{description}})", {
      amount: overrides[0].value,
      unit,
      description: overrides[0].description,
    });
  }

  function toTextAddition(result: {
    id: string;
    description: string;
    value: number | undefined;
  }) {
    return t("{{amount}}{{unit}} ({{description}})", {
      amount:
        result.value > 0
          ? "+" +
            formatNumber(
              type == TransformationValueType.Percent
                ? 100.0 * result.value
                : result.value,
            )
          : formatNumber(result.value),
      unit,
      description: result.description,
    });
  }

  function toTextMultiplier(result: {
    id: string;
    description: string;
    value: number | undefined;
  }) {
    return t("{{amount}} ({{description}})", {
      amount: formatMultiplier(result.value),
      description: result.description,
    });
  }

  const additions = explained.Addition.filter((value) => value.value != 0);
  const multipliers = explained.Multiplier.filter((value) => value.value != 1);

  if (additions.length == 0 && multipliers.length == 0) {
    return "";
  }

  const baseText = t("Base: {{amount}}{{unit}}", {
    amount: formatNumber(
      type == TransformationValueType.Percent
        ? 100.0 * startingValue
        : startingValue,
      { showDecimals: true },
    ),
    unit:
      type == TransformationValueType.Percent ? t("%{{unit}}", { unit }) : unit,
  });
  const additionText =
    additions.length > 0
      ? t("; Plus: {{plusText}}", {
          plusText: additions.map(toTextAddition).join(t(", ")),
        })
      : "";
  const multiplierText =
    multipliers.length > 0
      ? t("; Multipliers: {{plusText}}", {
          plusText: multipliers.map(toTextMultiplier).join(t(", ")),
        })
      : "";

  if (type == TransformationValueType.Log2) {
    const base = params?.base ?? 1.0;
    return t(
      `Results from multiplying {{base}} by the base 2 logarithm of the following: {{baseText}}{{additionText}}{{multiplierText}}`,
      {
        base: formatNumber(base, { showDecimals: true }),
        baseText,
        additionText,
        multiplierText,
      },
    );
  }

  if (type == TransformationValueType.Log2Divisor) {
    const base = params?.base ?? 1.0;
    return t(
      `Results from dividing by 1.0 + {{base}} times the base 2 logarithm of the following: {{baseText}}{{additionText}}{{multiplierText}}`,
      {
        base: formatNumber(base, { showDecimals: true }),
        baseText,
        additionText,
        multiplierText,
      },
    );
  }

  if (type == TransformationValueType.Divisor) {
    return t(
      `Results from dividing by 1.0 + the following: {{baseText}}{{additionText}}{{multiplierText}}`,
      {
        baseText,
        additionText,
        multiplierText,
      },
    );
  }
  return t("{{baseText}}{{additionText}}{{multiplierText}}", {
    baseText,
    additionText,
    multiplierText,
  });
}
