import { addRecords } from "../../../utils/CoreUtils";
import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { DoActionArgs } from "../../action/Action";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { simultaneousChanneling } from "../../events/storyline/pretraining/ChannelAllStoryline";
import { Storylines } from "../../storylines/Storylines";
import { TransformationTags } from "../../transformation/TransformationTags";
import { SpellElement } from "../Elements";
import { Spell } from "../Spell";
import ChannelAir from "./ChannelAir";
import ChannelEarth from "./ChannelEarth";
import ChannelFire from "./ChannelFire";
import ChannelWater from "./ChannelWater";

export const FOUR_CHANNELING_SPELLS = [
  ChannelFire,
  ChannelEarth,
  ChannelWater,
  ChannelAir,
];

class ChannelFour extends Spell {
  getId(): string {
    return "channelFour";
  }

  getSpellName(): string {
    return t("Channel Four");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      `Combines the effects of Channel Fire, Channel Earth, Channel Water, and Channel Air, all in one spell.`,
    );
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return {
      HOME: [ActionSubcategories.CHANNELING],
      SPELL_MENU: [
        SpellElement.Fire,
        SpellElement.Earth,
        SpellElement.Water,
        SpellElement.Air,
      ],
    };
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.CHANNELING;
  }

  getTags(): string[] {
    if (this._tagArray == null) {
      const tags = [
        TransformationTags.Spell,
        ...Object.keys(this.getLevelRequirements()),
      ];
      this._tagArray = tags;
    }
    return this._tagArray;
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: this.getManaCost(state),
      },
      items: {},
    };
  }

  getManaCost(state: GameState): number {
    return FOUR_CHANNELING_SPELLS.map((spell) =>
      spell.getManaCost(state, this),
    ).reduce((a, b) => a + b, 0);
  }

  getDisplayEffect(state: GameState): string {
    return FOUR_CHANNELING_SPELLS.map((spell) =>
      spell.getDisplayEffect(state),
    ).join(t(", "));
  }

  isVisible(state: GameState): boolean {
    return (
      hasFlag(state, Flag.ChannelingUnlocked) &&
      simultaneousChanneling.isBonusActive(state) &&
      !Storylines.getByIdNullable("allTheElements")?.isBonusActive?.(state)
    );
  }

  isEnabled(state: GameState, skipAffordabilityChecks?: boolean): boolean {
    return (
      super.isEnabled(state, skipAffordabilityChecks) &&
      this.isCastingRecommended(state)
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Fire: 1,
      Earth: 1,
      Water: 1,
      Air: 1,
    };
  }

  getExpFromUsage(state: GameState) {
    return FOUR_CHANNELING_SPELLS.reduce(
      (previous, current) =>
        addRecords([previous, current.getExpFromUsage(state)]),
      {},
    );
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    return FOUR_CHANNELING_SPELLS.reduce(
      (state, current) => current.doSpellAction(state, args),
      state,
    );
  }

  isCastingRecommended(state: GameState): boolean {
    return FOUR_CHANNELING_SPELLS.reduce(
      (previous, current) => previous || current.isCastingRecommended(state),
      false,
    );
  }

  getElement(): SpellElement | undefined {
    return undefined;
  }

  getBaseManaCost(state: GameState): number {
    throw new Error("Should not be called");
  }
}

export default new ChannelFour();
