import { t } from "i18next";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { TextInput } from "react-native-paper";
import { translateItemTag } from "../../backend/items/Item";
import { getAllKnownItemCompendiumInfos } from "../../backend/items/ItemCompendium";
import { ItemTagEnum } from "../../backend/items/ItemTagEnum";
import { Items } from "../../backend/items/Items";
import { ITEM_TAG_ORDER } from "../Transmute";
import {
  ItemGrid,
  ItemGridElement,
  ItemGridElementUsage,
} from "../inventory/ItemGrid";
import { Button } from "../utility/Button";
import { Picker as UtilityPicker } from "../utility/Picker";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { UIContext, useUIStateFromContext } from "../utility/UIContext";
import { useGameState } from "../utility/useGameState";
import { ItemCompendiumDetails } from "./ItemCompendiumDetails";

export default function ItemCompendium() {
  const state = useGameState();
  const uiContext = useContext(UIContext);
  const [selectedElement, setSelectedElement] = useState(
    null as ItemGridElement | null,
  );
  const [selectedTag, setSelectedTag] = useUIStateFromContext(
    uiContext,
    "CompendiumTag",
    "All",
  );
  let visibleItemInfos = getAllKnownItemCompendiumInfos(state);

  const tags = useMemo(() => {
    const tagSet = new Set<string>();
    visibleItemInfos.forEach((info) => {
      const tags = info.item.getTags();
      tags.forEach((tag) => tagSet.add(tag));
    });
    const tags = Array.from(tagSet).sort((a, b) => {
      const indexA = ITEM_TAG_ORDER.findIndex((tag) => tag == a);
      const indexB = ITEM_TAG_ORDER.findIndex((tag) => tag == b);
      return indexA - indexB;
    });
    return tags;
  }, [visibleItemInfos]);

  useEffect(() => {
    if (!tags.includes(selectedTag)) {
      setSelectedTag("All");
    }
  }, [tags.join(), selectedTag]);

  useEffect(() => setSelectedElement(null), [selectedTag]);

  const selectedElementRef = useRef(selectedElement);
  selectedElementRef.current = selectedElement;

  useEffect(() => {
    setSelectedElement(null);
  }, [visibleItemInfos]);

  const onPressItem = React.useCallback(
    (item: ItemGridElement) => {
      setSelectedElement(item);
    },
    [setSelectedElement],
  );

  const tagButtons = useMemo(() => {
    return (
      <UtilityPicker
        onValueChange={setSelectedTag}
        style={styles.picker}
        selectedValue={selectedTag}
        data={["All", ...tags].map((tag) => ({
          label: tag == "All" ? t("All") : translateItemTag(tag as ItemTagEnum),
          value: tag,
          key: tag,
        }))}
      />
    );
  }, [setSelectedTag, selectedTag, JSON.stringify(tags)]);

  const sortedItemInfos = useMemo(
    () =>
      visibleItemInfos
        .filter(
          (info) =>
            selectedTag === "All" ||
            info.item.getTags().includes(selectedTag as ItemTagEnum),
        )
        .sort((a, b) => {
          const firstTagA = ITEM_TAG_ORDER.findIndex((tag) =>
            a.item.getTags().includes(tag as ItemTagEnum),
          );
          const firstTagB = ITEM_TAG_ORDER.findIndex((tag) =>
            b.item.getTags().includes(tag as ItemTagEnum),
          );
          if (firstTagA != firstTagB) {
            return firstTagA - firstTagB;
          }
          if (a.transmuteSpell != null && b.transmuteSpell == null) {
            return 1;
          } else if (a.transmuteSpell == null && b.transmuteSpell != null) {
            return -1;
          } else if (a.transmuteSpell == null && b.transmuteSpell == null) {
            return 0;
          }

          const maxA = Math.max(
            ...(Object.values(a.transmuteSpell.getLevelRequirements()).filter(
              (n) => n != undefined,
            ) as number[]),
          );
          const maxB = Math.max(
            ...(Object.values(b.transmuteSpell.getLevelRequirements()).filter(
              (n) => n != undefined,
            ) as number[]),
          );
          return maxA - maxB;
        }),
    [selectedTag, visibleItemInfos],
  );

  const [filterText, setFilterText] = useState("");
  useEffect(() => setSelectedElement(null), [filterText]);

  const [filterTextTemp, setFilterTextTemp] = useState("");

  useEffect(() => {
    const timeout = setTimeout(() => setFilterText(filterTextTemp), 400);
    return () => clearTimeout(timeout);
  }, [filterTextTemp]);

  const itemGridElements = useMemo(
    () =>
      sortedItemInfos.map((info) => ({
        itemOrSpellId: info.item.getId(),
        amount: 1,
        params: info.item.getDefaultParams(),
        disabled: false,
        itemInfo: info,
      })),
    [sortedItemInfos],
  );

  const itemGridElementsFiltered = useMemo(() => {
    let elements = itemGridElements;
    if (filterText.length >= 3) {
      elements = elements.filter((itemGridElement) => {
        return Items.getById(itemGridElement.itemOrSpellId)
          .getDefaultName()
          .toLowerCase()
          .includes(filterText.toLowerCase());
      });
    }
    return elements.map((element, index) => ({
      ...element,
      disableTooltip: true,
    }));
  }, [itemGridElements, selectedElement, filterText]);

  const selectedItemInfo = selectedElement;

  const itemGrid = useMemo(
    () => (
      <ItemGrid
        items={itemGridElementsFiltered}
        location={ItemGridElementUsage.COMPENDIUM}
        onPressItem={onPressItem}
      />
    ),
    [itemGridElementsFiltered, onPressItem],
  );

  const details = useMemo(
    () => (
      <View style={styles.detailsContainer}>
        {selectedItemInfo && (
          <ItemCompendiumDetails info={selectedItemInfo.itemInfo} />
        )}
      </View>
    ),
    [selectedItemInfo?.itemOrSpellId],
  );

  const clearFilter = useCallback(() => {
    setFilterText("");
    setFilterTextTemp("");
  }, []);

  return useMemo(
    () => (
      <View style={styles.container}>
        <View style={[Styles.flexRowSpaceBetween, Styles.mb8, Styles.ph8]}>
          <View style={[styles.tagPickerContainer]}>{tagButtons}</View>
        </View>
        <View style={[Styles.flexRowAlignCenter, Styles.mb8, Styles.ph8]}>
          <Text>{t("Filter:")}</Text>
          <TextInput
            style={[Styles.ml8, { minWidth: 180 }]}
            mode="outlined"
            dense={true}
            value={filterTextTemp}
            onChangeText={setFilterTextTemp}
          />
          {filterTextTemp.length > 0 && (
            <Button onPress={clearFilter} compact={true} style={Styles.ml8}>
              {t("Clear")}
            </Button>
          )}
        </View>
        {itemGrid}
        {details}
      </View>
    ),
    [
      tagButtons,
      itemGrid,
      details,
      filterTextTemp,
      setFilterTextTemp,
      clearFilter,
    ],
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
    paddingTop: 16,
  },
  detailsContainer: {
    marginTop: 16,
    paddingHorizontal: 8,
    paddingBottom: 8,
  },
  buttonGroupContainer: {
    flexDirection: "row",
    alignItems: "flex-start",
    height: undefined,
    flexWrap: "wrap",
    justifyContent: "flex-start",
    borderWidth: 0,
  },
  buttonGroupButtonContainer: {
    flex: 1,
    height: 40,
    borderWidth: 1,
    borderColor: "#eee",
    margin: 4,
  },
  picker: {
    flex: 1,
  },
  tagPickerContainer: {
    width: Platform.OS != "web" ? 180 : null,
    ...Platform.select({
      android: { backgroundColor: "#eee", borderRadius: 10, default: {} },
    }),
  },
  row: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
    flexWrap: "wrap",
    marginBottom: 16,
    paddingHorizontal: 8,
  },
  checkboxContainer: {
    marginLeft: 0,
    padding: 0,
    maxWidth: 140,
  },
  checkboxText: {
    fontWeight: "normal",
  },
});
