import {
  ACHIEVEMENTS_SUPPORTED,
  unlockAchievementRaw,
} from "../utils/Achievements";
import { Achievement } from "./Achievement";
import { SetFlagListeners, hasEverHadFlag, hasFlag } from "./Flags";
import { GameState } from "./GameState";
import { addGameStateListener } from "./GameStateListeners";
import { addEnemyDeathListener } from "./exploration/Exploration";
import { Enemy, getCumulativeEnemyKills } from "./exploration/enemies/Enemy";
import { DataStore } from "./generic/DataStore";
import { Storyline } from "./storylines/Storyline";

const achievementsUnlocked: Partial<Record<Achievement, boolean>> = {};

export function registerAchievementUnlocker(
  checker: (state: GameState) => boolean,
  achievement: Achievement,
) {
  // Do not add achievements if not supported, to avoid all the calculations
  if (ACHIEVEMENTS_SUPPORTED) {
    addGameStateListener(achievement + "unlocker", (state) => {
      // Using local stored values for throttling rather than flags,
      // so that restarting the game will re-unlock achievements, just in case
      if (!achievementsUnlocked?.[achievement] && checker(state)) {
        unlockAchievement(achievement);
      }
    });
  }
}

// Prefer not to use as achievement sync is unsupported
export function unlockAchievement(achievement: Achievement) {
  if (!achievementsUnlocked?.[achievement]) {
    achievementsUnlocked[achievement] = true;
    if (ACHIEVEMENTS_SUPPORTED) {
      unlockAchievementRaw(achievement);
    }
  }
}

export const AchievementSyncListeners = new DataStore<
  (state: GameState) => void
>();

export function runAchievementSync(state: GameState) {
  if (ACHIEVEMENTS_SUPPORTED) {
    for (let listener of AchievementSyncListeners.getAll()) {
      listener(state);
    }
  }
}

export function registerAchievementUnlockerFromFlag(
  flag: string,
  achievement: Achievement,
) {
  if (ACHIEVEMENTS_SUPPORTED) {
    SetFlagListeners.register((state, flagSet) => {
      if (flagSet == flag && hasFlag(state, flagSet)) {
        unlockAchievement(achievement);
      }
      return state;
    }, achievement + "-achievementUnlocker");
    AchievementSyncListeners.register((state) => {
      if (hasEverHadFlag(state, flag)) {
        unlockAchievement(achievement);
      }
    }, achievement + "-syncUnlocker");
  }
}

export function registerAchievementUnlockerFromEnemy(
  enemy: Enemy,
  achievement: Achievement,
) {
  if (ACHIEVEMENTS_SUPPORTED) {
    addEnemyDeathListener(
      achievement + "-achievementUnlocker",
      (state: GameState, enemyDefeated: Enemy) => {
        if (enemyDefeated == enemy) {
          unlockAchievement(achievement);
        }
        return state;
      },
    );
    AchievementSyncListeners.register((state) => {
      if (getCumulativeEnemyKills(state, enemy.getId()) > 0) {
        unlockAchievement(achievement);
      }
    }, achievement + "-syncUnlocker");
  }
}

export function registerAchievementUnlockerFromStoryline(
  storyline: Storyline,
  achievement: Achievement,
) {
  registerAchievementUnlockerFromFlag(
    storyline.getCompletedFlag(),
    achievement,
  );
}
