import { t } from "../../utils/I18n";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../action/ActionAreas";
import { isAutocastUnlocked } from "../autocast/Autocast";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import { SpellElement } from "../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Building, BuildingSpell } from "./Building";
import { getBuildingAmount } from "./Buildings";

class WizardChamber extends Building {
  getId(): string {
    return "wizardChamber";
  }

  getName(): string {
    return t("Wizard Chamber");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A specially prepared area of your home that serves as a workstation for a hired Wizard.",
    );
  }

  getDisplayEffect(state: GameState): string {
    return t("+1 Wizard");
  }

  getBaseLandRequired(): number {
    return 3;
  }

  canTurnOff(): boolean {
    return false;
  }
}

const wizardChamber = new WizardChamber();

class BuildWizardChamber extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.OTHER] };
  }

  getBuilding(): Building {
    return wizardChamber;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Coins: 200,
      AirEssence: 120,
      FireEssence: 100,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      Coins: 1.25,
      AirEssence: 1.25,
      FireEssence: 1.25,
    };
  }

  isVisible(state: GameState): boolean {
    return isAutocastUnlocked(state) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }
}

const buildWizardChamber = new BuildWizardChamber();

registerTransformation(
  [[TransformationTags.WizardAmount]],
  "wizardChambers",
  t("Wizard Chambers"),
  TransformationType.Addition,
  (state, params) => getBuildingAmount(state, wizardChamber),
);

export {
  buildWizardChamber as BuildWizardChamber,
  wizardChamber as WizardChamber,
};
