import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectData,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class ResistAutoAttack extends TemporaryEffect {
  getId(): string {
    return "autoAttackProtection";
  }
  getDisplayName(): string {
    return t("Physical Barrier");
  }
  isBeneficial(): boolean {
    return true;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "{{amount}} damage received from physical attacks (auto-attacks)",
      {
        amount: formatMultiplier(temporaryEffectData.params.damageMultiplier),
      },
    );
  }
}

export const resistAutoAttack = new ResistAutoAttack();

export function loadResistAutoAttack() {
  TemporaryEffects.register(resistAutoAttack);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "autoAttackProtection",
    t("Physical Barrier"),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(
        state,
        resistAutoAttack.getId(),
        params.target,
      ) && (params.tags ?? []).includes(TransformationTags.AutoAttack)
        ? getTemporaryEffectData(state, resistAutoAttack.getId(), params.target)
            .params.damageMultiplier
        : 1.0;
    },
  );
}
