import memoize from "sonic-memoize";
import { fromEntries, keysOf, valuesOf } from "../../utils/CoreUtils";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { cacheBooleanTrueOnly, cacheIndefinitely } from "../Caching";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import { Action, DoActionArgs } from "../action/Action";
import { ActionArea, ActionSubcategory } from "../action/ActionAreas";
import {
  AutocastRule,
  SpellAutocastCategory,
  isSpellInPermaCast,
  isSpellInWizards,
} from "../autocast/Autocast";
import SmartlyAutocastCondition from "../autocast/conditions/SmartlyAutocastCondition";
import { hasTemporaryEffect } from "../temporaryeffects/TemporaryEffects";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  applyTransformations,
  applyTransformationsPermaCached,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import {
  SpellElement,
  getElementExpByUsage,
  grantElementExp,
  meetsRequirements,
} from "./Elements";
import { SpellCastListeners } from "./SpellCastListeners";
import { addRecentSpellCast, setLastCastedTime } from "./Spells";
import { isCastingRecommendedTemporaryEffect } from "./Utils";

export abstract class Spell extends Action {
  abstract getBaseManaCost(state: GameState): number;
  abstract doSpellAction(state: GameState, args: DoActionArgs): GameState;
  abstract getLevelRequirements(): Partial<Record<SpellElement, number>>;
  abstract getElement(): SpellElement | undefined;

  // Translated version of the spell name.
  abstract getSpellName(): string;

  abstract getAutocastCategory(): SpellAutocastCategory;

  constructor() {
    super();
  }

  _tagArray: string[] | null = null;

  getTags(): string[] {
    if (this._tagArray == null) {
      const tags = [
        ...super.getTags(),
        TransformationTags.Spell,
        ...Object.keys(this.getLevelRequirements()),
        ...this.getExtraTags(),
      ];
      const element = this.getElement();
      tags.push(element);
      this._tagArray = tags;
    }
    return this._tagArray;
  }

  getExtraTags(): string[] {
    return [];
  }

  _manaCostTags = memoize((parentSpell?: Spell) =>
    [
      ...this.getTags(),
      TransformationTags.Cost,
      Resource.Mana,
      parentSpell?.getId?.(),
    ].filter((tag) => tag != null),
  );

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: this.getManaCost(state),
      },
      items: {},
    };
  }

  getManaCost(state: GameState, parentSpell?: Spell) {
    if (parentSpell != null) {
      return this.getManaCostUncached(state, parentSpell);
    }

    let value = state.temp.spellManaCostCache[this.getId()];
    if (value == null) {
      value = this.getManaCostUncached(state, parentSpell);
      state.temp.spellManaCostCache[this.getId()] = value;
    }
    return value;
  }

  private getManaCostUncached(state: GameState, parentSpell?: Spell): number {
    return applyTransformations(
      this._manaCostTags(parentSpell),
      state,
      this.getBaseManaCost(state),
      parentSpell == null
        ? this._actionParams()
        : { action: parentSpell ?? this },
    );
  }

  // Override with false if you change getManaCost()
  hasFixedManaCost(): boolean {
    return true;
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return {
      SPELL_MENU: [this.getElement()],
    };
  }

  getLastCastTime(state: GameState): number {
    return state.run.lastSpellCastTimes?.[this.getId()] || 0;
  }

  /**
   * Display name without cooldown or perma-cast
   */
  getBasicDisplayName(state: GameState): string {
    let mainName = this.getSpellName();
    if (this.hasActiveSpellcrafts(state)) {
      mainName = mainName + "*";
    }
    if (this.isEmpowered(state)) {
      mainName = mainName + "+";
    }
    return mainName;
  }

  getDisplayName(state: GameState): string {
    const lastCastTime = this.getLastCastTime(state);
    const isInPermaCast = isSpellInPermaCast(state, this);
    let mainName = this.getBasicDisplayName(state);

    if (lastCastTime > 0) {
      const cooldown = lastCastTime + this.getCooldown(state);
      const secTillOver = cooldown - getSecondsPlayed(state);
      if (secTillOver > 0) {
        mainName = t("{{spellName}} ({{secTillOver}} sec)", {
          spellName: mainName,
          secTillOver: formatNumber(secTillOver, {
            showDecimals: true,
          }),
        });
      }
    }
    if (isInPermaCast) {
      return t("{{mainName}} (P)", { mainName });
    } else if (isSpellInWizards(state, this)) {
      return t("{{mainName}} (A)", { mainName });
    }
    return t("{{mainName}}", { mainName });
  }

  hasActiveSpellcrafts(state: GameState): boolean {
    return valuesOf(state.run.spellcrafts).some(
      (data) => data.spellId == this.getId() && data.spellcraftId != null,
    );
  }

  getBaseCooldown(): number {
    return 0.0;
  }

  _cooldownTagArray: string[] | null = null;
  _actionParams = memoize(() => ({ action: this }));
  getCooldown(state: GameState): number {
    if (this._cooldownTagArray == null) {
      this._cooldownTagArray = [
        ...this.getTags(),
        TransformationTags.SpellCooldown,
      ];
    }
    if (this.getBaseCooldown() === 0.0) {
      return 0.0;
    }
    return applyTransformationsPermaCached(
      this._cooldownTagArray,
      state,
      this.getBaseCooldown(),
      this._actionParams(),
    );
  }

  isVisible(state: GameState): boolean {
    const id = this.getId();
    let value = state.temp.spellRequirementsCache[id];
    if (value == null) {
      const elementRequirements = this.getLevelRequirements();
      value = meetsRequirements(state, elementRequirements);
      state.temp.spellRequirementsCache[id] = value;
    }
    return value;
  }

  isEnabled(state: GameState, skipAffordabilityChecks?: boolean): boolean {
    if (
      this._cacheEnabled == null ||
      this._cacheEnabled.ticks != state.global.tickCount
    ) {
      const enabled = this._isEnabledNoCache(state, skipAffordabilityChecks);
      this._cacheEnabled = { value: enabled, ticks: state.global.tickCount };
    }
    return this._cacheEnabled.value;
  }

  protected shouldIgnoreCanCastSpell() {
    return false;
  }

  _isEnabledNoCache(state: GameState, skipAffordabilityChecks?: boolean) {
    if (!this.isVisible(state)) {
      return false;
    }

    const lastCastTime = this.getLastCastTime(state);
    if (
      lastCastTime > 0 &&
      getSecondsPlayed(state) < lastCastTime + this.getCooldown(state)
    ) {
      return false;
    }

    if (!this.shouldIgnoreCanCastSpell()) {
      if (
        hasTemporaryEffect(state, "silence") ||
        hasTemporaryEffect(state, "stun")
      ) {
        return false;
      }

      const transformationResult = applyTransformationsPermaCached(
        this.canCastSpellTags(),
        state,
        1.0,
      );
      if (transformationResult <= 0) {
        return false;
      }
    }

    return super.isEnabled(state, skipAffordabilityChecks);
  }

  _cacheEnabled: { value: boolean; ticks: number } | null = null;

  isPermaCastAllowed(): boolean {
    return (
      this.getBaseCooldown() > 0 ||
      (this.getCachedBaseActionEffects()?.duration?.value ?? 0) > 0
    );
  }

  getPermaCastDelaySec(state: GameState): number {
    return this.getEstimatedMinAutocastDelay(state);
  }

  getPermaCastManaCostPerSec(state: GameState): number {
    return this.getManaCost(state) / this.getPermaCastDelaySec(state);
  }

  getPermaCastAutocastPointsConsumptionPerSec(state: GameState): number {
    return (
      this.getAutocastPointsRequirement(state) /
      this.getPermaCastDelaySec(state)
    );
  }

  canCastSpellTags = memoize(() => [
    ...this.getTags(),
    TransformationTags.CanCastSpell,
  ]);

  autocastPointsRequirementTags = memoize(() => [
    ...this.getTags(),
    TransformationTags.AutocastPointsConsumption,
  ]);

  getAutocastPointsRequirement(state: GameState): number {
    let value = state.temp.autocastPointsRequirementCache[this.getId()];
    if (value == null) {
      value = applyTransformations(
        this.autocastPointsRequirementTags(),
        state,
        this.getBaseAutocastPointsRequirement(),
        this._actionParams(),
      );
      state.temp.autocastPointsRequirementCache[this.getId()] = value;
    }
    return value;
  }

  getBaseAutocastPointsRequirement(): number {
    return 60;
  }

  getExpFromUsage(state: GameState): Partial<Record<SpellElement, number>> {
    const id = this.getId();
    let value = state.temp.spellExpCache[id];
    if (value == null) {
      const elementRequirements = this.getLevelRequirements();
      value = fromEntries(
        keysOf(elementRequirements).map((element) => {
          return [
            element,
            getElementExpByUsage(
              state,
              element,
              elementRequirements[element],
              this.getCost(state).resources?.[Resource.Mana] ?? 0,
              true,
              this.getTags(),
            ),
          ];
        }),
      );
      state.temp.spellExpCache[id] = value;
    }
    return value;
  }

  getMainExp(state: GameState): {
    element: SpellElement | undefined;
    expMin: number;
    expMax: number;
  } {
    const exp = this.getExpFromUsage(state);
    const element = this.getElement();
    if (element != null) {
      return {
        element,
        expMin: exp[element] ?? 0,
        expMax: exp[element] ?? 0,
      };
    }

    return {
      element: undefined,
      expMin: Math.min(...valuesOf(exp)),
      expMax: Math.max(...valuesOf(exp)),
    };
  }

  grantExpFromUsage(state: GameState): GameState {
    const exp = this.getExpFromUsage(state);
    const expElements = keysOf(exp);

    expElements.forEach((element) => {
      state = grantElementExp(element, exp[element])(state);
    });
    return state;
  }

  doActionPermaCast(state: GameState): GameState {
    state = this.doSpellAction(state, { isAutomatic: true });
    state = this.grantExpFromUsage(state);
    state = setLastCastedTime(this, state);
    for (let listener of SpellCastListeners.getAll()) {
      state = listener(state, this);
    }
    return state;
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }

    state = this.doSpellAction(state, args);

    state = this.payCost(state);

    state = this.grantExpFromUsage(state);

    if (!args.isAutomatic) {
      state = addRecentSpellCast(this, state);
    }

    state = setLastCastedTime(this, state);

    for (let listener of SpellCastListeners.getAll()) {
      state = listener(state, this);
    }

    return state;
  }

  isCastingRecommended(state: GameState): boolean {
    if (!isCastingRecommendedTemporaryEffect(state, this.getId())) {
      return false;
    }
    return true;
  }

  getEstimatedMinAutocastDelay(state: GameState, rule?: AutocastRule): number {
    if (
      rule != null &&
      rule.conditions.length > 0 &&
      rule.conditions.every(
        (condition) => condition.condition != SmartlyAutocastCondition.getId(),
      )
    ) {
      // We cannot predict
      return Math.max(0, this.getCooldown(state));
    }

    let duration = 0;
    const baseEffects = this.getCachedBaseActionEffects();
    if (baseEffects?.duration) {
      duration = cacheIndefinitely(
        state,
        (_state) => "minAutocastDelayDuration" + this.getId(),
        (state) => this.getActionEffect(state, "duration"),
      );
    }
    return Math.max(
      0,
      duration > 0 ? Math.max(1, Math.floor(duration - 2)) : 0,
      this.getCooldown(state),
    );
  }

  protected isEmpowerable(): boolean {
    return false;
  }

  getEmpoweringLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  isEmpoweringPossible(state: GameState): boolean {
    if (!this.isEmpowerable()) {
      return false;
    }

    return cacheBooleanTrueOnly(
      state,
      "spellEmpoweringEnabled." + this.getId(),
      (s) => {
        const elementRequirements = this.getEmpoweringLevelRequirements();
        const isVisible = meetsRequirements(s, elementRequirements);
        return isVisible;
      },
    );
  }

  isEmpowered(state: GameState): boolean {
    return state.run.empoweredSpells[this.getId()] ?? false;
  }

  allowRegisteringTemporaryEffectsAsPermacast(): boolean {
    return true;
  }

  // // Is this spell dependent on the effects of others?
  // // If yes, override and put the spells here.
  // // Spellcraft uses this to delegate its effects.
  // getDependentSpells(): Spell[] {
  //   return [];
  // }
}
