import { valuesOf } from "../../utils/CoreUtils";
import { formatMultiplier, formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import {
  getStatFromTarget,
  modifyTargetCurrentHP,
} from "../exploration/Exploration";
import { ExplorationStatus } from "../exploration/ExplorationStatus";
import { getExplorationStatus } from "../exploration/ExplorationUtils";
import { GameState } from "../GameState";
import { registerTimeTickListener } from "../timetick/TimeTick";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectData,
  getTemporaryEffects,
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Berserk extends TemporaryEffect {
  getId(): string {
    return "berserk";
  }
  getDisplayName(): string {
    return t("Berserk");
  }
  isBeneficial(): boolean {
    return true;
  }
  getDamagePerSec(state: GameState, temporaryEffectData: TemporaryEffectData) {
    const target = temporaryEffectData.target;
    const maxHP = getStatFromTarget(state, target, CombatStat.MaxHP);
    const fraction = temporaryEffectData.params.damageMagnitude;
    return maxHP * fraction;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("{{multiplier}}:attack:; -{{amount}}:hp:/sec while in combat", {
      multiplier: formatMultiplier(temporaryEffectData.params.attackMultiplier),
      amount: formatNumber(this.getDamagePerSec(state, temporaryEffectData), {
        showDecimals: true,
      }),
    });
  }
}

export const berserk = new Berserk();

export function loadBerserk() {
  TemporaryEffects.register(berserk);

  registerTimeTickListener("BerserkDoT", (state, timeElapsedSec) => {
    const temporaryEffects = getTemporaryEffects(
      state,
      (state, effect) => effect.id == berserk.getId(),
    );
    temporaryEffects.forEach((effect) => {
      if (getExplorationStatus(state) == ExplorationStatus.Combat) {
        state = modifyTargetCurrentHP(
          state,
          effect.target,
          -berserk.getDamagePerSec(state, effect) * timeElapsedSec,
          t("Berserk"),
        );
      }
    });
    return state;
  });

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[target, CombatStat.Attack]],
      "berserk" + target,
      berserk.getDisplayName(),
      TransformationType.Multiplier,
      (state) => {
        if (!hasTemporaryEffect(state, berserk.getId(), target)) {
          return 1.0;
        }
        return getTemporaryEffectData(state, berserk.getId(), target).params
          .attackMultiplier;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    berserk.getId(),
    CombatStat.Attack,
  );
}
