import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import VenomPowder from "../../../items/material/VenomPowder";
import { disease } from "../../../temporaryeffects/Disease";
import { poison } from "../../../temporaryeffects/Poison";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import { calculateAttackTarget } from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class SludgeMass extends Enemy {
  getId(): string {
    return "sludgeMass";
  }

  getName(): string {
    return t("Sludge Mass");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 6500,
      attack: 47,
      defense: 10,
      attackDelay: 4.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./sludgeMass.png");
  }

  isBoss(): boolean {
    return true;
  }

  getLevel(): number {
    return 17;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 199;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 110;
  }

  getNextAction(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (!hasTemporaryEffect(state, disease.getId(), target)) {
      return {
        name: t("Plague"),
        delay: this.getAttackDelay(state),
        transform: (state) => {
          const target = calculateAttackTarget(state);
          state = grantTemporaryEffect(disease.getId(), 25, {
            target,
          })(state);
          return state;
        },
      };
    }
    if (
      !hasTemporaryEffect(state, poison.getId(), target) &&
      Math.random() < 0.5
    ) {
      return {
        name: t("Poison"),
        delay: this.getAttackDelay(state),
        transform: (state) => {
          const target = calculateAttackTarget(state);
          state = grantTemporaryEffect(poison.getId(), 12, {
            target,
            params: {
              magnitude: this.getAttack(state) / 7,
            },
          })(state);
          return state;
        },
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: VenomPowder.getId(),
        amount: 2,
        chance: 1.0,
      },
    ];
  }

  getTips(): string {
    return t(
      `What is this *monstrosity*?! It really, *really* stinks! It is so powerful that it's leaving us sick, making us unable to get healthy again! On top of that, it is also highly poisonous! But, at least, we can try and prevent that. Let's make sure we go prepared with something to counter the poison, like with a Charm of Poison Resistance (requires Poison Lv4), and ready to heal as soon as the disease subsides.`,
    );
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "sewerHazardousSector");
    return state;
  }
}

export default new SludgeMass();
