import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { getResourceAmount, getResourceCap, Resource } from "../../Resources";
import { Spell } from "../../spells/Spell";
import {
  AutocastCondition,
  AutocastConditionParameter,
} from "../AutocastCondition";
import { NumericAutocastConditionParameterType } from "./parameters/NumericAutocastConditionParameterType";
import { ResourceAutocastConditionParameterType } from "./parameters/ResourceAutocastConditionParameterType";

class ResourceLessThanAutocastConditionRatio extends AutocastCondition {
  getId(): string {
    return "IfResourceAmountLessThanRatio";
  }

  getDisplayName(): string {
    return t("If held resource is less than % of max");
  }

  getParameters(): AutocastConditionParameter[] {
    return [
      {
        parameterType: new ResourceAutocastConditionParameterType(),
        name: t("Resource"),
      },
      {
        parameterType: new NumericAutocastConditionParameterType(0, 100),
        name: t("Percent"),
      },
    ];
  }

  getDisplayExplanation(state: GameState, values: string[]): string {
    return t("If less than {{amount}}% of the max {{resourceIcon}} are held", {
      amount: formatNumber(Number.parseFloat(values[1])),
      resourceIcon: `:${values[0].toLowerCase()}:`,
    });
  }

  isConditionMet(
    state: GameState,
    spell: Spell | undefined,
    values: string[],
  ): boolean {
    const resource = values[0] as Resource;
    const percent = Number.parseFloat(values[1]);
    return (
      getResourceAmount(state, resource) / getResourceCap(state, resource) <
      percent / 100.0
    );
  }
}

export default new ResourceLessThanAutocastConditionRatio();
