import memoize from "sonic-memoize";
import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource, clearResourceCapCache } from "../../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { SpellElement } from "../../spells/Elements";
import { clearManaCostCache } from "../../spells/Spells";
import {
  TransformationType,
  applyTransformations,
  explainTransformationsText,
  registerTransformation,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import { BuildingAmountListeners, getBuildingAmount } from "../Buildings";

const BASE_BONUS = 100;
const perBuildingCapTags = [
  TransformationTags.PerBuildingCap,
  Resource.Mana,
  "manaShard",
];
function getMaxManaBonus(state: GameState): number {
  return applyTransformations(perBuildingCapTags, state, BASE_BONUS, {
    resource: Resource.Mana,
  });
}
const explanationParams = memoize(() => ({
  resource: Resource.Mana,
  unit: t(" Max:mana:"),
}));
function getMaxManaBonusExplanation(state: GameState): string {
  return explainTransformationsText(
    perBuildingCapTags,
    state,
    BASE_BONUS,
    explanationParams(),
  );
}

class ManaShard extends Building {
  getId(): string {
    return "manaShard";
  }

  getName(): string {
    return t("Mana Shard");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A small furniture that allows you to accumulate more Mana, to cast more powerful spells.",
    );
  }

  getDisplayEffect(state: GameState): string {
    return t("^+{{maxMana}}^<{{explanation}}> Max:mana:", {
      maxMana: getMaxManaBonus(state),
      explanation: getMaxManaBonusExplanation(state),
    });
  }

  getBaseLandRequired(): number {
    return 2;
  }

  canTurnOff(): boolean {
    return false;
  }
}

const manaShard = new ManaShard();

class BuildManaShard extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.MANA] };
  }

  getBuilding(): Building {
    return manaShard;
  }

  shouldNeverHaveDemolishBlockers(): boolean {
    return false;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      EarthEssence: 40,
      WaterEssence: 20,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      EarthEssence: 1.25,
      WaterEssence: 1.25,
    };
  }

  getBaseManaCost(state: GameState): number {
    return 10;
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.ManaShardsUnlocked) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Earth;
  }
}

const buildManaShard = new BuildManaShard();

registerTransformation(
  [[TransformationTags.Cap, Resource.Mana]],
  "manaShards",
  t("Mana Shards"),
  TransformationType.Addition,
  (state, params) =>
    getBuildingAmount(state, manaShard) * getMaxManaBonus(state),
);

BuildingAmountListeners.register((state, building) => {
  if (building == manaShard) {
    state = clearResourceCapCache(state);
    state = clearManaCostCache(state);
  }
  return state;
}, manaShard.getId());

export { buildManaShard as BuildManaShard, manaShard as ManaShard };
