import { t } from "../../../../utils/I18n";
import { Flag } from "../../../Flag";
import { hasFlag } from "../../../Flags";
import { GameState } from "../../../GameState";
import { Enemy } from "../../enemies/Enemy";
import EvolvedSlime from "../../enemies/sewers/EvolvedSlime";
import Goblin from "../../enemies/sewers/Goblin";
import MutatedFish from "../../enemies/sewers/MutatedFish";
import PoisonousToad from "../../enemies/sewers/PoisonousToad";
import SewerFish from "../../enemies/sewers/SewerFish";
import {
  DungeonFloor,
  encounterEnemy,
  ExplorationPossibility,
} from "../DungeonFloor";
import SewerHazardousSector from "./SewerHazardousSector";

const EXPLORATION_POSSIBILITIES = [
  {
    weight: 100,
    transforms: encounterEnemy(EvolvedSlime),
  },
  {
    weight: 70,
    transforms: encounterEnemy(PoisonousToad),
  },
  {
    weight: 70,
    transforms: encounterEnemy(SewerFish),
  },
  {
    weight: 10,
    transforms: encounterEnemy(Goblin),
  },
];
class SewerEntrance extends DungeonFloor {
  getId(): string {
    return "sewerEntrance";
  }
  getFloorName(): string {
    return t("Sewer Entrance");
  }
  getBoss(state: GameState): Enemy {
    return MutatedFish;
  }
  getBreedingLevel(): number {
    return 10;
  }
  isMainStoryDungeonFloor(): boolean {
    return true;
  }
  getExplorationRequiredTimeSecBase(): number {
    return 5.0;
  }
  _getExplorationSuccessesForBossBase(): number {
    return 30;
  }
  getBaseExplorationPossibilities(state: GameState): ExplorationPossibility[] {
    return EXPLORATION_POSSIBILITIES;
  }
  getNextFloor(state: GameState): DungeonFloor {
    return SewerHazardousSector;
  }
  isUnlocked(state: GameState): boolean {
    return hasFlag(state, Flag.SewersUnlocked);
  }
}

export default new SewerEntrance();
