import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { GameState } from "../GameState";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectData,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class WizardDisarray extends TemporaryEffect {
  getId(): string {
    return "wizardDisarray";
  }
  getDisplayName(): string {
    return t("Wizard Disarray");
  }
  getIcon() {
    return require("./wizardDisarray.png");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("{{amount}}:wizardpower: production", {
      amount: formatMultiplier(temporaryEffectData.params.multiplier),
    });
  }
}

export const wizardDisarray = new WizardDisarray();

export function loadWizardDisarray() {
  TemporaryEffects.register(wizardDisarray);

  registerTransformation(
    [[TransformationTags.AutocastPointsProduction]],
    wizardDisarray.getId(),
    wizardDisarray.getDisplayName(),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(
        state,
        wizardDisarray.getId(),
        AttackTarget.Player,
      )
        ? getTemporaryEffectData(
            state,
            wizardDisarray.getId(),
            AttackTarget.Player,
          ).params.multiplier
        : 1.0;
    },
  );
}
