import { Entypo } from "@expo/vector-icons";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import {
  Platform,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
} from "react-native";
import { useTheme } from "react-native-paper";
import { Action } from "../../backend/action/Action";
import { isAutoPurchaseUnlocked } from "../../backend/action/Actions";
import { isAutocastUnlocked } from "../../backend/autocast/Autocast";
import { BuildingSpell } from "../../backend/buildings/Building";
import {
  changeBuildingsTurnedOn,
  getBuildingAmount,
  getBuildingAmountTurnedOn,
  getFreeLand,
} from "../../backend/buildings/Buildings";
import { Spell } from "../../backend/spells/Spell";
import { t } from "../../utils/I18n";
import { useAuxOptions } from "./AuxOptionsContext";
import { Button } from "./Button";
import { CheckBox } from "./CheckBox";
import { CostRow, CostViewer } from "./CostViewer";
import { Markdown } from "./Markdown";
import { DescriptionViewer } from "./Miscellaneous";
import { PlusMinusAllocator } from "./PlusMinusAllocator";
import { SpellWizardsInfo } from "./SpellWizardsInfo";
import { useExpandedUIFromContext } from "./StatePersistedExpandedUI";
import Styles from "./Styles";
import { Text } from "./Text";
import { TooltipPositionType, TooltipWrapper } from "./Tooltip";
import { VisibilityContext } from "./VisibilityContext";
import { useGameState } from "./useGameState";

export function ActionContainer(props: { action: Action }) {
  const theme = useTheme();
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const action = props.action;
  const cost = action.getCost(state);
  const isFree =
    Object.keys(cost.resources).length === 0 &&
    Object.keys(cost.items).length === 0;
  const canTurnOff = action instanceof BuildingSpell && action.canTurnOff();
  const buildingAmount =
    action instanceof BuildingSpell &&
    getBuildingAmount(state, action.getBuilding());
  const buildingAmountOn =
    action instanceof BuildingSpell &&
    getBuildingAmountTurnedOn(state, action.getBuilding());
  const landRequirement =
    action instanceof BuildingSpell
      ? action.getBuilding().getLandRequired(state)
      : null;
  const freeLand = getFreeLand(state);
  const capIsEnough = action.isCapEnough(state);

  const [expanded, setExpanded] = useExpandedUIFromContext(
    state,
    action.getId(),
    false,
  );

  const descriptionContainer = useMemo(() => {
    const descriptionContents = `${action.getDisplayDescription(
      state,
    )} **${action.getDisplayEffect(state)}**`;
    return (
      <View style={styles.descriptionTextContainer}>
        <DescriptionViewer style={Styles.flex}>
          {descriptionContents}
        </DescriptionViewer>
      </View>
    );
  }, [
    (expanded || Platform.OS === "web") && action.getDisplayDescription(state),
    (expanded || Platform.OS === "web") && action.getDisplayEffect(state),
  ]);

  const landContainer = useMemo(() => {
    if (!landRequirement) {
      return null;
    }

    return (
      <View style={styles.costContainer}>
        <Markdown>{t("Land required:")}</Markdown>
        <View style={Styles.mt4}>
          <CostRow
            cap={freeLand}
            current={freeLand}
            cost={landRequirement}
            icon="land"
            name={t("Land")}
            switch={true}
          />
        </View>
      </View>
    );
  }, [landRequirement, freeLand]);

  const costContainer = useMemo(() => {
    return (
      <View style={styles.costContainer}>
        <Markdown>{t("Cost:")}</Markdown>
        <View style={Styles.mt4}>
          <CostViewer
            resources={action.getCost(state).resources}
            items={action.getCost(state).items}
          />
        </View>
      </View>
    );
  }, [(expanded || Platform.OS === "web") && JSON.stringify(cost)]);

  const onPressMinus = useCallback(
    () =>
      stateRef.current.apply((s) =>
        changeBuildingsTurnedOn((action as BuildingSpell).getBuilding(), -1, s),
      ),
    [action],
  );
  const onPressPlus = useCallback(
    () =>
      stateRef.current.apply((s) =>
        changeBuildingsTurnedOn((action as BuildingSpell).getBuilding(), 1, s),
      ),
    [action],
  );

  const turnOffContainer = useMemo(() => {
    if (!canTurnOff) {
      return null;
    }
    const amountActiveText = `${buildingAmountOn}/${buildingAmount}`;
    return (
      <View style={styles.turnOnRow}>
        <Markdown>{t("Amount active:")}</Markdown>
        <View style={styles.turnOnQuantityContainer}>
          <Markdown>{amountActiveText}</Markdown>
          <View style={styles.plusMinusContainer}>
            <PlusMinusAllocator
              disableMinus={buildingAmountOn === 0}
              disablePlus={buildingAmountOn === buildingAmount}
              onPressMinus={onPressMinus}
              onPressPlus={onPressPlus}
            />
          </View>
        </View>
      </View>
    );
  }, [canTurnOff, buildingAmount, buildingAmountOn, onPressMinus, onPressPlus]);

  const demolish = useCallback(
    () =>
      stateRef.current.apply((s) => (action as BuildingSpell).demolish(s, 1)),
    [action],
  );

  const demolishBlockers = useMemo(
    () =>
      action instanceof BuildingSpell
        ? action.getDemolishBlockers(state, 1)
        : [],
    [expanded && state, action],
  );

  const canDemolish = useMemo(
    () =>
      action instanceof BuildingSpell ? action.canDemolish(state, 1) : true,
    [expanded && state, action],
  );

  const demolishContainer = useMemo(() => {
    if (
      buildingAmount &&
      buildingAmount > 0 &&
      landRequirement != null &&
      landRequirement > 0 &&
      !action.shouldHideDemolish(state)
    ) {
      return (
        <View style={Styles.mt8}>
          <Button
            mode="outlined"
            compact={true}
            onPress={demolish}
            enableAutoRepress={action.canSpam()}
            disabled={!canDemolish}
            style={[Styles.alignSelfNormal, Styles.mt4]}
          >
            {t("Dispose of 1 (+{{land}} Land)", { land: landRequirement })}
          </Button>
          {demolishBlockers.length > 0 && (
            <View style={Styles.mt4}>
              <Text style={{ color: theme.colors.error }}>
                {t(
                  "You may not dispose of this furniture because without it you wouldn't have been able to build the following furniture: {{furnitureList}}. To dispose of it, dispose of those other furniture first.",
                  {
                    furnitureList: demolishBlockers
                      .map((blocker) => blocker.getName())
                      .join(t(", ")),
                  },
                )}
              </Text>
            </View>
          )}
        </View>
      );
    }
    return null;
  }, [
    buildingAmount,
    landRequirement,
    theme,
    demolish,
    demolishBlockers.map((building) => building.getId()).join(),
    action instanceof BuildingSpell && action.shouldHideDemolish(state),
    action.canSpam(),
  ]);

  const id = action.getId();
  const canAutoPurchase =
    isAutoPurchaseUnlocked(state) && action.canAutoPurchaseMax();
  const isAutoPurchaseEnabled = state.run.autoPurchaseActions?.[id];
  const autoPurchaseEnabledToggle = useCallback(
    () =>
      stateRef.current.apply((state) => {
        state.run.autoPurchaseActions[id] = !(
          state.run.autoPurchaseActions[id] ?? false
        );
        return state;
      }),
    [id],
  );

  const autoPurchaseContainer = useMemo(
    () =>
      canAutoPurchase ? (
        <CheckBox
          title={t("Auto purchase max")}
          checked={isAutoPurchaseEnabled}
          onPress={autoPurchaseEnabledToggle}
          containerStyle={Styles.mt4}
        />
      ) : null,
    [canAutoPurchase, isAutoPurchaseEnabled, autoPurchaseEnabledToggle],
  );

  const disabled = !action.isEnabled(state);

  const [preConfirmation, setPreConfirmation] = useState(false);

  const visible = useContext(VisibilityContext);

  useEffect(() => {
    if (!visible) {
      setPreConfirmation(false);
    }
  }, [visible]);

  const onPressCallback = useCallback(() => {
    if (action.requireConfirmation(state) && !preConfirmation) {
      setPreConfirmation(true);
    } else {
      stateRef.current.apply(
        action.doAction.bind(action, { isAutomatic: false }),
      );
      setPreConfirmation(false);
    }
  }, [preConfirmation]);

  const isAutoPurchasing = state.run.autoPurchaseActions[action.getId()];

  const onPressExpand = useCallback(
    () => setExpanded(!expanded),
    [setExpanded, expanded],
  );

  const auxOptions = useAuxOptions();

  const button = useMemo(
    () => (
      <TooltipWrapper
        tooltip={
          !expanded ? (
            <View>
              {descriptionContainer}
              {landContainer}
              {!isFree && costContainer}
            </View>
          ) : undefined
        }
        tooltipId={action.getId() + "actioncontainer"}
        tooltipPosition={TooltipPositionType.RIGHT_OF_ELEMENT}
      >
        <Button
          key={action.getId()}
          onPress={onPressCallback}
          enableAutoRepress={true}
          style={styles.buttonContainer}
          labelStyle={!capIsEnough && { textDecorationLine: "line-through" }}
          disabled={disabled}
        >
          {preConfirmation
            ? t("Press again to confirm")
            : t("{{actionName}}{{autoPurchase}}", {
                actionName: action.getDisplayName(state),
                autoPurchase: isAutoPurchasing ? t(" (A)") : "",
              })}
        </Button>
      </TooltipWrapper>
    ),
    [
      id,
      action.getDisplayName(state),
      capIsEnough,
      disabled,
      onPressCallback,
      descriptionContainer,
      isFree,
      landContainer,
      costContainer,
      expanded,
      preConfirmation,
      isAutoPurchasing,
    ],
  );

  // Hack *only* for channeling spells
  const showWizards =
    action instanceof Spell && !(action instanceof BuildingSpell);

  return useMemo(() => {
    return (
      <View
        style={[
          styles.actionContainer,
          auxOptions.compactLayout && { marginVertical: 4 },
        ]}
        key={action.getId()}
      >
        <View style={styles.buttonRowContainer}>
          <View style={styles.buttonContainerView}>{button}</View>
          <TouchableWithoutFeedback
            onPress={onPressExpand}
            accessibilityLabel={!expanded ? "Expand" : "Collapse"}
          >
            <View style={styles.expandContainer}>
              <Entypo
                name={!expanded ? "chevron-right" : "chevron-down"}
                size={24}
                color={theme.colors.onBackground}
              />
            </View>
          </TouchableWithoutFeedback>
        </View>
        {expanded && (
          <View style={[Styles.mt8, Styles.mb8]}>
            <View style={Styles.mt8}>{descriptionContainer}</View>
            {landContainer}
            {!isFree && costContainer}
            {canTurnOff && turnOffContainer}
            {autoPurchaseContainer}
            {demolishContainer}
            {showWizards && isAutocastUnlocked(state) ? (
              <View style={Styles.mt4}>
                <SpellWizardsInfo spellId={action.getId()} />
              </View>
            ) : null}
          </View>
        )}
      </View>
    );
  }, [
    button,
    id,
    disabled,
    canTurnOff,
    turnOffContainer,
    auxOptions.compactLayout,
    expanded && costContainer,
    expanded && descriptionContainer,
    expanded && demolishContainer,
    expanded && autoPurchaseContainer,
    expanded,
    onPressExpand,
    theme,
    isAutocastUnlocked(state),
    showWizards,
  ]);
}

const styles = StyleSheet.create({
  actionContainer: { marginVertical: 8 },
  buttonRowContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  quickbarButtonContainer: {
    marginLeft: 8,
  },
  expandContainer: {
    width: 64,
    flexDirection: "row",
    justifyContent: "center",
    alignSelf: "stretch",
    alignItems: "center",
    cursor: "pointer",
  },
  costContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start",
    marginTop: 4,
  },
  descriptionTextContainer: {
    flex: 1,
    flexDirection: "row",
    flexWrap: "wrap",
  },
  buttonTitle: {
    fontSize: 16,
    textAlign: "center",
    paddingVertical: 1,
    ...Platform.select({
      android: {
        fontFamily: "sans-serif-medium",
      },
      default: {
        fontSize: 18,
      },
    }),
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  buttonContainerView: {
    flexDirection: "row",
    flex: 1,
  },
  buttonContainer: {
    flex: 1,
  },
  buttonContainerSmall: {
    marginRight: 0,
  },
  capTooLowText: {
    color: "#d99",
  },
  turnOnRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    marginTop: 8,
  },
  turnOnQuantityContainer: {
    flexDirection: "row",
    alignItems: "center",
  },
  plusMinusContainer: {
    marginLeft: 8,
  },
  spellButtonTitleContainer: {
    flex: 1,
    flexDirection: "row",
  },
  spellButtonTitleOnlyContainer: {
    flex: 1,
  },
  spellButtonCostContainer: {
    flexDirection: "row",
    width: 40,
    justifyContent: "flex-end",
  },
  spellManaIcon: {
    height: 16,
    width: 16,
    justifyContent: "flex-end",
    alignItems: "center",
    top: 1,
    marginLeft: 2,
  },
  marginTop: {
    marginTop: 8,
  },
});
