import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { ActionEffect } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";
import { registerStandardEmpowerEffects } from "../Spells";
import {
  getStandardSpellAttackEffect,
  getStandardSpellAttackEffectText,
} from "../Utils";

class StoneStrike extends CombatSpellBase {
  getId(): string {
    return "stoneStrike";
  }

  getSpellName(): string {
    return t("Stone Strike");
  }

  getElement(): SpellElement {
    return SpellElement.Earth;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.ATTACK;
  }

  getExtraTags(): string[] {
    return [TransformationTags.AttackSpell];
  }

  getBaseManaCost(state: GameState): number {
    return 210;
  }

  getDisplayDescription(state: GameState): string {
    return t("Shower your enemy with multiple small rocks, dealing damage.");
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      average: {
        value: 60,
        tags: [TransformationTags.ActionMagnitude],
        unit: ":attack:",
      },
      variance: {
        value: 0,
        tags: [TransformationTags.ActionVariance],
      },
    };
  }

  getDisplayEffect(state: GameState): string {
    return getStandardSpellAttackEffectText(state, this);
  }

  doSpellAction(state: GameState): GameState {
    return getStandardSpellAttackEffect(state, this);
  }

  getBaseCooldown(): number {
    return 2.0;
  }

  getBaseAutocastPointsRequirement(): number {
    return 10;
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Earth: 6,
    };
  }

  protected isEmpowerable(): boolean {
    return true;
  }

  getEmpoweringLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Earth: 47 };
  }
}

const stoneStrike = new StoneStrike();

registerStandardEmpowerEffects(stoneStrike, 4, 10);

export default stoneStrike as StoneStrike;
