import { t } from "../../../../../utils/I18n";
import { AttackTarget } from "../../../../exploration/AttackTarget";
import { CombatStat } from "../../../../exploration/CombatStats";
import { GameState } from "../../../../GameState";
import { Resource } from "../../../../Resources";
import { SpellElement } from "../../../../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../../../../transformation/Transformation";
import { EquipmentSlot, getEquippedItem } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class TunicOfGoodHealth extends EquippableItem {
  getId(): string {
    return "tunicOfGoodHealth";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Body;
  }
  getPicture() {
    return require("./tunicOfGoodHealth.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Tunic of Good Health");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A cotton-based cloth armor that greatly boosts longevity and allows for recovering quicker.",
    );
  }
  getEffectExtra(state: GameState, params: ItemParams): string {
    return t("Double passive HP regeneration speed");
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 4800;
  }

  getDefenseBonusBase(params: ItemParams): number {
    return 24;
  }

  getMaxHPBonusBase(params: ItemParams): number {
    return 80;
  }
}

export const tunicOfGoodHealth = new TunicOfGoodHealth();

registerTransformation(
  [[CombatStat.HPRegen, AttackTarget.Player]],
  tunicOfGoodHealth.getId(),
  tunicOfGoodHealth.getName(tunicOfGoodHealth.getDefaultParams()),
  TransformationType.Multiplier,
  (state) =>
    getEquippedItem(state, EquipmentSlot.Body)?.itemId ==
    tunicOfGoodHealth.getId()
      ? 2.0
      : 1.0,
);

class TransmuteTunicOfGoodHealth extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return tunicOfGoodHealth;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Water: 19,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 500,
        WaterEssence: 30000,
      },
      items: {
        elementalShardWater: 60,
        cotton: 20,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Water;
  }
}

export const transmuteTunicOfGoodHealth = new TransmuteTunicOfGoodHealth();
