import moment from "moment";
import React from "react";
import { StyleSheet, View } from "react-native";
import { EndingStats } from "../../backend/GameState";
import { Challenges } from "../../backend/challenges/Challenges";
import { CombatClasses } from "../../backend/classes/CombatClasses";
import { Endings } from "../../backend/ending/Endings";
import { translateElement } from "../../backend/spells/Elements";
import { getTotalStorylineCount } from "../../backend/storylines/Storylines";
import { formatNumber, formatTimeLong } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Text } from "./Text";

type RowProps = {
  caption: string;
  value: string;
};

function Row(props: RowProps) {
  return (
    <View style={styles.row}>
      <View style={styles.rowCaption}>
        <Text numberOfLines={2}>{props.caption}</Text>
      </View>
      <Text style={styles.valueText}>{props.value}</Text>
    </View>
  );
}

export function EndingDataViewer(props: { endingData: EndingStats }) {
  const endingData = props.endingData;
  const ending = Endings.getById(endingData.endingId);

  const totalDuration = moment.duration(endingData.totalTimePlayed, "seconds");
  const thisWorldDuration = moment.duration(
    endingData.totalTimePlayedThisWorld,
    "seconds",
  );
  const thisRunDuration = moment.duration(
    endingData.totalTimePlayedThisRun,
    "seconds",
  );
  const totalActiveDuration =
    endingData.totalActiveTimePlayed != null
      ? moment.duration(endingData.totalActiveTimePlayed, "seconds")
      : null;
  const totalActiveDurationThisWorld =
    endingData.totalActiveTimePlayedThisWorld != null
      ? moment.duration(endingData.totalActiveTimePlayedThisWorld, "seconds")
      : null;
  const totalActiveDurationThisRun =
    endingData.totalActiveTimePlayedThisRun != null
      ? moment.duration(endingData.totalActiveTimePlayedThisRun, "seconds")
      : null;
  const thisRunTimePieces = endingData.timePiecesConsumedThisRun;
  const thisWorldTimePieces = endingData.timePiecesConsumedThisWorld;
  const totalTimePieces = endingData.timePiecesConsumed;
  const totalRealDuration =
    endingData.totalRealWorldTimePlayed != null
      ? moment.duration(endingData.totalRealWorldTimePlayed, "seconds")
      : null;
  const totalRealDurationThisWorld =
    endingData.totalRealWorldTimePlayedThisWorld != null
      ? moment.duration(endingData.totalRealWorldTimePlayedThisWorld, "seconds")
      : null;
  const totalRealDurationThisRun =
    endingData.totalRealWorldTimePlayedThisRun != null
      ? moment.duration(endingData.totalRealWorldTimePlayedThisRun, "seconds")
      : null;

  const combatClassText =
    CombatClasses.getByIdNullable(
      endingData.combatClassId,
    )?.getDisplayName?.() ?? t("None");

  const primaryElementText =
    endingData.secondaryElement != null
      ? t("{{primaryElement}}, {{secondaryElement}}", {
          primaryElement: translateElement(endingData.primaryElement),
          secondaryElement: translateElement(endingData.secondaryElement),
        })
      : t("{{primaryElement}}", {
          primaryElement: translateElement(endingData.primaryElement),
        });

  const primaryElementLevelsText =
    endingData.secondaryElement != null
      ? t("{{primaryElementLevel}}, {{secondaryElementLevel}}", {
          primaryElementLevel: formatNumber(
            endingData.primaryElementLevelOnEnding,
          ),
          secondaryElementLevel: formatNumber(
            endingData.secondaryElementLevelOnEnding,
          ),
        })
      : t("{{primaryElementLevel}}", {
          primaryElementLevel: formatNumber(
            endingData.primaryElementLevelOnEnding,
          ),
        });

  const challengeText = Challenges.getByIdNullable(
    endingData.challengeId,
  )?.getName?.();

  const newGamePlusCount = endingData.worldsCreated;
  const isNewGamePlus = newGamePlusCount > 0;
  const showWorldStats = isNewGamePlus;
  const worldModifiers = t(
    "{{sheerPower}}/{{storylineOracle}}/{{blazingSpeed}} - D{{worldDifficulty}}",
    {
      sheerPower: formatNumber(endingData.sheerPowerLevel),
      storylineOracle: formatNumber(endingData.storylineOracleLevel),
      blazingSpeed: formatNumber(endingData.blazingSpeedLevel),
      worldDifficulty: formatNumber(endingData.worldDifficultyLevel),
    },
  );

  const storylineCount = getTotalStorylineCount();

  return (
    <View>
      <Row caption={t("Ending:")} value={ending.title} />
      <Row
        caption={t("Date:")}
        value={moment(endingData.timestamp).format("Y-MM-DD h:mm A")}
      />
      {isNewGamePlus && (
        <Row caption={t("World Modifiers:")} value={worldModifiers} />
      )}
      {challengeText != null && (
        <Row caption={t("Challenge:")} value={challengeText} />
      )}
      <Row caption={t("Primary Element:")} value={primaryElementText} />
      <Row caption={t("Class:")} value={combatClassText} />
      <Row
        caption={t("Total In-Game Time Played:")}
        value={formatTimeLong(totalDuration)}
      />
      {showWorldStats && (
        <Row
          caption={t("In-Game Time Played (This World):")}
          value={formatTimeLong(thisWorldDuration)}
        />
      )}
      <Row
        caption={t("In-Game Time Played (This Run):")}
        value={formatTimeLong(thisRunDuration)}
      />
      <Row
        caption={t("Total Real World Time Played:")}
        value={formatTimeLong(totalActiveDuration)}
      />
      {showWorldStats && (
        <Row
          caption={t("Real World Time Played (This World):")}
          value={formatTimeLong(totalActiveDurationThisWorld)}
        />
      )}
      <Row
        caption={t("Real World Time Played (This Run):")}
        value={formatTimeLong(totalActiveDurationThisRun)}
      />
      <Row
        caption={t("Total Time Pieces Consumed:")}
        value={formatNumber(totalTimePieces)}
      />
      {showWorldStats && (
        <Row
          caption={t("Time Pieces Consumed (This World):")}
          value={formatNumber(thisWorldTimePieces)}
        />
      )}
      <Row
        caption={t("Time Pieces Consumed (This Run):")}
        value={formatNumber(thisRunTimePieces)}
      />
      <Row
        caption={t("Total Time Since Starting Game:")}
        value={formatTimeLong(totalRealDuration)}
      />
      {showWorldStats && (
        <Row
          caption={t("Total Time Since Starting This World:")}
          value={formatTimeLong(totalRealDurationThisWorld)}
        />
      )}
      <Row
        caption={t("Total Time Since Starting This Run:")}
        value={formatTimeLong(totalRealDurationThisRun)}
      />
      {showWorldStats && (
        <Row
          caption={t("Other Worlds visited:")}
          value={formatNumber(newGamePlusCount)}
        />
      )}
      <Row
        caption={
          showWorldStats
            ? t("Total Retirements (This World):")
            : t("Total Retirements:")
        }
        value={endingData.totalRetirements?.toString?.() ?? ""}
      />
      <Row
        caption={t("Maximum Primary Level Obtained:")}
        value={endingData.mpl.toString()}
      />
      <Row
        caption={t("Primary Element Level on Game Beaten:")}
        value={primaryElementLevelsText}
      />
      <Row
        caption={t("Global Experience Multiplier:")}
        value={t("{{multiplier}}x", {
          multiplier: formatNumber(endingData.researchMultiplier, {
            showDecimals: true,
          }),
        })}
      />
      <Row
        caption={t("Global Production Multiplier:")}
        value={t("{{multiplier}}x", {
          multiplier: formatNumber(endingData.productionMultiplier, {
            showDecimals: true,
          }),
        })}
      />
      <Row
        caption={t("Total Completed Storylines:")}
        value={t("{{completedStorylines}} / {{totalStorylines}}", {
          completedStorylines: formatNumber(endingData.completedStorylines),
          totalStorylines: storylineCount,
        })}
      />
      <Row
        caption={t("Storyline Completion Percentage:")}
        value={t("{{percent}}%", {
          percent: formatNumber(
            endingData.completedStorylines > storylineCount
              ? 100 + (endingData.completedStorylines - storylineCount)
              : (100 * endingData.completedStorylines) / storylineCount,
          ),
        })}
      />
    </View>
  );
}

const styles = StyleSheet.create({
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: 4,
  },
  valueText: {
    minWidth: 100,
    textAlign: "right",
    fontWeight: "bold",
    marginLeft: 8,
  },
  rowCaption: {
    flex: 1,
  },
});
