import { t } from "../../../../utils/I18n";
import { Flag } from "../../../Flag";
import { setFlag } from "../../../Flags";
import { addGameStateListener } from "../../../GameStateListeners";
import { registerPostRetirementListener } from "../../../Retire";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

export const convenienceStoryline = new Storyline(
  "convenience",
  t("Convenience"),
  327,
  10,
  (state) => t("Expand your home at least 20 times"),
  (state) =>
    t('Unlock "Auto purchase max" option for furniture and "Expand Home"'),
  (state) => true,
);

export function loadConvenience() {
  Storylines.register(convenienceStoryline);
  const eventBuilder = buildEvent("convenience", t("(Storyline) Convenience"), [
    EventTag.STORYLINE,
  ]).setStorylineIds(convenienceStoryline.getId());

  eventBuilder.message(
    t(`Your home has come a long way since you started working towards obtaining the Philosopher's Stone. It is now much, much bigger than it initially was - you think it is at least five times larger!
    
And yet, even with all the furniture you have, it feels almost as if you could never have enough...`),
  );
  eventBuilder
    .message(
      t(`The process of building furniture and expanding your home has left you frankly quite tired. If only there was a way to make it at least a little easier...
    
...even if just a little...

...*wait*, you think there *might* be a way!`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = convenienceStoryline.complete(state);
          state = convenienceStoryline.activateBonus(state);
          state = setFlag(Flag.AutoPurchaseUnlocked)(state);
          return state;
        },
      },
      "end",
    );
  eventBuilder
    .message(
      t(`Of course. Magic spells are difficult, but... you could definitely make building furniture and expanding your home *happen automatically*! Why didn't you think of this before?
      
You write down the idea in your diary. You can't wait to apply it... right away.

**You have completed the "Convenience" Storyline! You have permanently unlocked a feature to automatically build furniture, expand your home or purchase Enhancements (and a few other things) once you can afford to. You can toggle this feature for each individual furniture in the Home screen, by expanding its details. Tip: this is especially useful for Catalysts!**`),
    )
    .tag("end");

  const event = eventBuilder.build();

  addGameStateListener(event.getId(), (state) => {
    if (
      !event.hasTriggered(state) &&
      !convenienceStoryline.isCompleted(state) &&
      state.run.landPurchaseCount >= 20
    ) {
      return triggerEvent(event);
    }
  });

  registerPostRetirementListener(
    convenienceStoryline.getId(),
    (state, isMock) => {
      if (convenienceStoryline.isBonusActive(state)) {
        state = setFlag(Flag.AutoPurchaseUnlocked)(state);
      }
      return state;
    },
  );
}
