import { isElectron } from "../../utils/ElectronUtils";
import { t } from "../../utils/I18n";
import { isDemo } from "../Demo";
import { Flag } from "../Flag";
import { SetFlagListeners } from "../Flags";
import { buildEvent } from "./EventMessageBuilder";
import { triggerEvent } from "./Events";

export const getDemoAboutText = () =>
  isElectron()
    ? t(
        "Get the full version for Windows on [Steam](https://store.steampowered.com/app/2864890/Magic_Research_2/?utm_source=demo_about_steam)!",
      )
    : t(
        "Get the full version for Windows on [Steam](https://store.steampowered.com/app/2864890/Magic_Research_2/?utm_source=demo_about_web), for Android in [the Google Play Store](https://play.google.com/store/apps/details?id=com.mcolotto.magicresearchtwo&utm_source=demo_about_web) or for iOS in [the App Store](https://apps.apple.com/app/id6478566840)!",
      );

const fullGameLink = () =>
  isElectron()
    ? t(
        "[**Get the full game now!**](https://store.steampowered.com/app/2864890/Magic_Research_2/?utm_source=demo_end_steam)",
      )
    : t(
        "**Get the full game now for Windows on [Steam](https://store.steampowered.com/app/2864890/Magic_Research_2/?utm_source=demo_end_web), for Android in [the Google Play Store](https://play.google.com/store/apps/details?id=com.mcolotto.magicresearchtwo&utm_source=demo_end_web) or for iOS in [the App Store](https://apps.apple.com/app/id6478566840)!**",
      );

export const getMR1AboutText = () =>
  isElectron()
    ? t(
        "If you like the game, you may also like the original [Magic Research](https://store.steampowered.com/app/2311680/Magic_Research/?utm_source=mr2_about_steam)!",
      )
    : t(
        "If you like the game, you may also like the original **Magic Research**, for Windows on [Steam](https://store.steampowered.com/app/2311680/Magic_Research/?utm_source=mr2_about_web), for Android in [the Google Play Store](https://play.google.com/store/apps/details?id=com.mcolotto.magicresearch&utm_source=mr2_about_web) or for iOS in [the App Store](https://apps.apple.com/app/id1671272877)!",
      );

export function loadDemoEndEvent() {
  // Do nothing
  const eventBuilder = buildEvent("demoEnd", t("End of the Demo"), []);

  eventBuilder.message(
    t(
      `Congratulations! You have defeated the Man-Eating Plant. This is the end of the Demo Version of Magic Research 2. **Thank you for playing!**
  
In the full game, you can expect much more content: more places to explore, more spells and items, and plenty more secrets to discover! Your progress in the Demo can be easily transferred to the full game.

*Will you be able to successfully heal the deadly disease with the Philosopher's Stone?*

{{fullGameLink}}`,
      { fullGameLink: fullGameLink() },
    ),
  );

  const event = eventBuilder.build();

  if (isDemo()) {
    SetFlagListeners.register((state, flag) => {
      if (flag == Flag.VastDesertUnlocked && !event.hasTriggered(state)) {
        state = triggerEvent(event)(state);
      }
      return state;
    });
  }
}
