import memoize from "sonic-memoize";
import { fromEntries } from "../../../utils/CoreUtils";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { sewageSystemEnhancement } from "../../enhancement/other/SewageSystem";
import { SpellElement, getUnlockedElements } from "../../spells/Elements";
import { registerSpell } from "../../spells/Spells";
import { RESOURCE_FOR_ELEMENT } from "../../spells/channeling/ChannelingResources";
import {
  IncomeOverTimeProducer,
  IncomeOverTimeProducers,
  clearCalculatedIncomeCache,
} from "../../timetick/IncomeOverTime";
import {
  applyTransformations,
  explainTransformationsText,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import {
  BuildingAmountListeners,
  Buildings,
  getBuildingAmountTurnedOn,
} from "../Buildings";

const productionTags = memoize((resource) => [
  TransformationTags.Production,
  resource,
  "sewageSystem",
]);
const unitForResource = memoize((resource) => ({
  unit: t("{{resourceIcon}}/sec", {
    resourceIcon: `:${resource.toLowerCase()}:`,
  }),
}));
const BASE_PRODUCTION = 100.0;
function getEssenceProductionPerSec(
  state: GameState,
  resource: Resource,
): number {
  return applyTransformations(productionTags(resource), state, BASE_PRODUCTION);
}
function getEssenceProductionPerSecExplanation(
  state: GameState,
  resource: Resource,
): string {
  return explainTransformationsText(
    productionTags(resource),
    state,
    BASE_PRODUCTION,
    unitForResource(resource),
  );
}

const poisonTags = [
  TransformationTags.Consumption,
  Resource.PoisonEssence,
  "sewageSystem",
];
const BASE_CONSUMPTION = 10.0;
function getPoisonEssenceConsumptionPerSec(state: GameState): number {
  return applyTransformations(poisonTags, state, BASE_CONSUMPTION);
}
const unitForConsumption = memoize(() => ({
  unit: t(":poisonessence:/sec"),
}));
function getPoisonEssenceConsumptionPerSecExplanation(
  state: GameState,
): string {
  return explainTransformationsText(
    poisonTags,
    state,
    BASE_CONSUMPTION,
    unitForConsumption(),
  );
}

function getResourcesProduced(state: GameState): Resource[] {
  let baseEssences = [
    Resource.FireEssence,
    Resource.EarthEssence,
    Resource.WaterEssence,
    Resource.AirEssence,
  ];
  if (hasFlag(state, Flag.SewageSystemMakesAll)) {
    baseEssences = getUnlockedElements(state)
      .filter((element) => element != SpellElement.Poison)
      .map((element) => RESOURCE_FOR_ELEMENT[element]);
  }
  if (hasFlag(state, "enhancement-sewageParadox")) {
    baseEssences.push(Resource.PoisonEssence);
  }
  return baseEssences;
}

class SewageSystem extends Building {
  getId(): string {
    return "sewageSystem";
  }

  getName(): string {
    return t("Sewage System");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A nifty piece of furniture that converts Poison Essence into other elemental essences automatically.",
    );
  }

  getDisplayEffect(state: GameState): string {
    const resourcesProduced = getResourcesProduced(state);
    const resourceStrings = [];
    for (let resource of resourcesProduced) {
      resourceStrings.push(
        t("^+{{income}}^<{{explanation}}>:{{icon}}:/sec", {
          income: formatNumber(getEssenceProductionPerSec(state, resource), {
            showDecimals: true,
          }),
          explanation: getEssenceProductionPerSecExplanation(state, resource),
          icon: resource.toLowerCase(),
        }),
      );
    }
    const productionString = resourceStrings.join(t(", "));
    if (hasFlag(state, "enhancement-sewageParadox")) {
      return productionString;
    }
    return t(
      "{{productionString}}; ^-{{consumption}}^<{{consumptionExplanation}}>:poisonessence:/sec",
      {
        productionString,
        consumption: formatNumber(getPoisonEssenceConsumptionPerSec(state), {
          showDecimals: true,
        }),
        consumptionExplanation:
          getPoisonEssenceConsumptionPerSecExplanation(state),
      },
    );
  }

  getBaseLandRequired(): number {
    return 6;
  }

  canTurnOff(): boolean {
    return true;
  }
}

export const sewageSystem = new SewageSystem();

class BuildSewageSystem extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.OTHER] };
  }

  getBuilding(): Building {
    return sewageSystem;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      PoisonEssence: 6000,
      WaterEssence: 5000,
      AirEssence: 5000,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      PoisonEssence: 1.25,
      WaterEssence: 1.25,
      AirEssence: 1.25,
    };
  }

  isVisible(state: GameState): boolean {
    return sewageSystemEnhancement.isPurchased(state) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Poison;
  }
}

export const buildSewageSystem = new BuildSewageSystem();

export function loadSewageSystem() {
  Buildings.register(sewageSystem);
  registerSpell(buildSewageSystem);

  IncomeOverTimeProducers.register(
    new IncomeOverTimeProducer(
      sewageSystem.getId(),
      sewageSystem.getName(),
      (state) => {
        const resources = getResourcesProduced(state);
        const resourcesPerSec = fromEntries(
          resources.map((resource) => [
            resource,
            getEssenceProductionPerSec(state, resource) *
              getBuildingAmountTurnedOn(state, sewageSystem),
          ]),
        );
        if (!hasFlag(state, "enhancement-sewageParadox")) {
          resourcesPerSec.PoisonEssence =
            -1 *
            getPoisonEssenceConsumptionPerSec(state) *
            getBuildingAmountTurnedOn(state, sewageSystem);
        }
        return resourcesPerSec;
      },
    ),
  );

  BuildingAmountListeners.register((state, building) => {
    if (building == sewageSystem) {
      state = clearCalculatedIncomeCache(state);
    }
    return state;
  });
}
