import Plugin from "markdown-it-regexp";

const plugin = Plugin(
  // regexp to match
  /\^(.+?)\^<(.*?)>/,

  // this function will be called when something matches
  (match: any[], utils: any) => {
    const base = match[1];
    const tooltip = match[2];

    return [base, tooltip];
  },
);

export default plugin;
