import { t } from "../../../../utils/I18n";
import {
  getAutocastStatusByRule,
  getCurrentAutocastStrategy,
} from "../../../autocast/Autocast";
import { Flag } from "../../../Flag";
import { setFlag } from "../../../Flags";
import { addGameStateListener } from "../../../GameStateListeners";
import { registerPostRetirementListener } from "../../../Retire";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

export const permacastStoryline = new Storyline(
  "permaCast",
  t("Perma-Cast"),
  295,
  10,
  (state) =>
    t(
      "Have at least 15 rules in your Wizards' current strategy that each have at least 1 Wizard assigned",
    ),
  (state) => t('Unlock "Perma-Cast" option for spells'),
  (state) => true,
);

export function loadPermaCast() {
  Storylines.register(permacastStoryline);
  const eventBuilder = buildEvent("permaCast", t("(Storyline) Perma-Cast"), [
    EventTag.STORYLINE,
  ]).setStorylineIds(permacastStoryline.getId());

  eventBuilder
    .message(
      t(`One more spell to be cast by Wizards! You watch as your :mana: reserves empty with each spell cast, only to quickly recover and go down again.
    
Your set of instructions is growing more and more complicated, and yet, you are starting to realize that many of them are simply: "cast this spell as often as necessary or possible".`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = permacastStoryline.complete(state);
          state = permacastStoryline.activateBonus(state);
          state = setFlag(Flag.PermaCastUnlocked)(state);
          return state;
        },
      },
      "end",
    );
  eventBuilder
    .message(
      t(`Those are much simpler instructions. You realize it should be a lot easier to give these instructions and quickly come up with a method. And you're just about ready to try it out...

**You have completed the "Perma-Cast" Storyline! You have permanently unlocked a feature to "Perma-Cast" many spells, including things like buffs, or anything with a cooldown. You may set spells to "Perma-Cast" by expanding the spell's details in the Spell menu, and otherwise manage these spells in the Wizards screen!**`),
    )
    .tag("end");

  const event = eventBuilder.build();

  addGameStateListener(event.getId(), (state) => {
    if (
      !event.hasTriggered(state) &&
      !permacastStoryline.isCompleted(state) &&
      getCurrentAutocastStrategy(state).rules.filter(
        (rule) => getAutocastStatusByRule(state, rule)?.wizardsAssigned > 0,
      ).length >= 15
    ) {
      return triggerEvent(event);
    }
  });

  registerPostRetirementListener(
    permacastStoryline.getId(),
    (state, isMock) => {
      if (permacastStoryline.isBonusActive(state)) {
        state = setFlag(Flag.PermaCastUnlocked)(state);
      }
      return state;
    },
  );
}
