import { AutocastConditions } from "../AutocastCondition";
import AlwaysAutocastCondition from "./AlwaysAutocastCondition";
import ResourceLessThanAutocastCondition from "./ResourceLessThanAutocastCondition";
import ResourceLessThanAutocastConditionRatio from "./ResourceLessThanAutocastConditionRatio";
import ResourceMoreThanAutocastCondition from "./ResourceMoreThanAutocastCondition";
import ResourceMoreThanAutocastConditionRatio from "./ResourceMoreThanAutocastConditionRatio";
import SmartlyAutocastCondition from "./SmartlyAutocastCondition";
import TemporaryEffectLowTimeRemainingAutocastCondition from "./TemporaryEffectLowTimeRemainingAutocastCondition";
import TemporaryEffectPresentAutocastCondition from "./TemporaryEffectPresentAutocastCondition";

export function loadAutocastConditions() {
  AutocastConditions.register(AlwaysAutocastCondition);
  AutocastConditions.register(SmartlyAutocastCondition);
  AutocastConditions.register(ResourceLessThanAutocastCondition);
  AutocastConditions.register(ResourceLessThanAutocastConditionRatio);
  AutocastConditions.register(ResourceMoreThanAutocastCondition);
  AutocastConditions.register(ResourceMoreThanAutocastConditionRatio);
  AutocastConditions.register(TemporaryEffectPresentAutocastCondition);
  AutocastConditions.register(TemporaryEffectLowTimeRemainingAutocastCondition);
}
