import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Cherry } from "../../../items/equipment/pouch/Cherry";
import Wildgrass from "../../../items/material/Wildgrass";
import { standardEnemyAttackEffect } from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class Goat extends Enemy {
  getId(): string {
    return "goat";
  }

  getName(): string {
    return t("Goat");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 180,
      attack: 18,
      defense: 0,
      attackDelay: 2.8,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./goat.png");
  }

  getLevel(): number {
    return 2;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 14;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 3;
  }

  getNextAction(state: GameState): BattlerAction {
    if (Math.random() < 0.1) {
      return {
        name: t("Baa!"),
        delay: this.getAttackDelay(state),
        transform: (state) => state,
      };
    }
    if (Math.random() < 0.25) {
      return {
        name: t("Strong Attack"),
        delay: this.getAttackDelay(state) * 2.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 2.0);
          return state;
        },
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 3,
        chance: 0.15,
      },
      {
        itemId: Cherry.getId(),
        amount: 1,
        chance: 0.005,
      },
    ];
  }
}

export default new Goat();
