import { useCallback } from "react";
import { electron, isElectron } from "../../utils/ElectronUtils";
import { KeyboardListener } from "./KeyboardListener";

export function FullScreenListener() {
  if (!isElectron()) {
    return null;
  }

  const callback = useCallback((e: KeyboardEvent) => {
    if (e.key == "F11" || (e.key == "Enter" && e.altKey)) {
      electron().ipcRenderer.send("toggleFullscreen");
    }
  }, []);

  return <KeyboardListener callback={callback} />;
}
