import React, { useCallback, useEffect, useMemo, useState } from "react";
import { StyleSheet, View } from "react-native";
import {
  getUnreadNotificationsByGroup,
  markNotificationAsRead,
} from "../backend/Notifications";
import {
  Help,
  HelpCategory,
  getVisibleHelpsByCategory,
  translateHelpCategory,
} from "../backend/helps/Helps";
import { t } from "../utils/I18n";
import CategoryContainer from "./utility/CategoryContainer";
import Divider from "./utility/Divider";
import { HelpContainer } from "./utility/HelpContainer";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

function HelpsCategoryContainer(props: {
  expandedHelp: string;
  setHelpCallback: (helpTitle: string) => void;
  helps: Help[];
  category: HelpCategory;
}) {
  const { category, expandedHelp, helps, setHelpCallback } = props;

  const state = useGameState();
  const helpNotifications = getUnreadNotificationsByGroup(state, "Help").map(
    (notification) => notification.item,
  );

  const helpNameString = helps.map((help) => help.title).join(",");

  const helpContainers = useMemo(
    () =>
      helps.map((help) => {
        return (
          <HelpContainer
            key={help.title}
            help={help}
            showBadge={helpNotifications.includes(help.id)}
            collapsed={expandedHelp != help.id}
            onPressExpand={setHelpCallback.bind(null, help.id)}
          />
        );
      }),
    [helpNameString, expandedHelp, setHelpCallback, helpNotifications.join()],
  );

  return useMemo(() => {
    if (helps.length === 0) {
      return null;
    }
    return (
      <CategoryContainer title={translateHelpCategory(category)}>
        <View style={Styles.mt4}>
          {helpContainers.map((helpContainer, idx) => {
            return (
              <View key={helps[idx].title}>
                {idx > 0 && <Divider />}
                {helpContainer}
              </View>
            );
          })}
          <Divider />
        </View>
      </CategoryContainer>
    );
  }, [helpNameString, helpContainers]);
}

function HelpsContainer() {
  const [expandedHelp, setExpandedHelp] = useState("");

  const state = useGameState();
  const stateRef = React.useRef(state);
  stateRef.current = state;

  const setHelpCallback = useCallback(
    (id: string) => {
      if (expandedHelp == id) {
        setExpandedHelp("");
      } else {
        setExpandedHelp(id);
      }
    },
    [expandedHelp],
  );

  useEffect(() => {
    if (expandedHelp != "") {
      stateRef.current.apply((state) =>
        markNotificationAsRead(state, "Help", expandedHelp),
      );
    }
  }, [expandedHelp]);

  const allHelps = getVisibleHelpsByCategory(state);

  return useMemo(() => {
    let categoryContainers = [];
    for (let category in allHelps) {
      const helps = allHelps[category];
      if (helps != null && helps.length > 0) {
        categoryContainers.push(
          <HelpsCategoryContainer
            category={category as HelpCategory}
            expandedHelp={expandedHelp}
            helps={helps}
            setHelpCallback={setHelpCallback}
            key={category}
          />,
        );
      }
    }
    return (
      <View>
        {categoryContainers}
        <Divider />
      </View>
    );
  }, [JSON.stringify(allHelps), expandedHelp, setExpandedHelp]);
}

export default function HelpScreen() {
  return (
    <View>
      <View style={styles.headerContainer}>
        <Text h1>{t("Help")}</Text>
      </View>

      <Text>{t("Learn how to play the game!")}</Text>
      <View style={styles.helpContainer}>
        <HelpsContainer />
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  headerContainer: {
    marginBottom: 16,
  },
  valueText: {
    minWidth: 60,
    textAlign: "right",
    marginLeft: 8,
  },
  valueTextLong: {
    minWidth: 112,
  },
  helpContainer: {
    marginTop: 16,
  },
  captionText: {
    flex: 1,
  },
});
