import { useCallback, useEffect, useRef, useState } from "react";
import { cleanup } from "../../backend/Cleanup";
import { hasFlag } from "../../backend/Flags";
import { GameState } from "../../backend/GameState";
import {
  nextFrame,
  processOfflineTicks,
  processTimeTick,
  processWarpTime,
} from "../../backend/timetick/TimeTick";
import {
  TransformationType,
  applyTransformations,
  registerTransformation,
} from "../../backend/transformation/Transformation";
import { TransformationTags } from "../../backend/transformation/TransformationTags";
import { t } from "../../utils/I18n";
import { useGameState } from "../utility/useGameState";
// import {
//   clearTimeout,
//   setInterval,
//   setTimeout,
// } from "../utility/FailSafeTimers";

const TARGET_FPS = 20;

registerTransformation(
  [[TransformationTags.GameSpeed]],
  "CombatPausedMagicTap",
  t("Combat paused for accessibility"),
  TransformationType.Multiplier,
  (state) => (hasFlag(state, "combat_paused_magic_tap") ? 0 : 1),
);

const gameSpeedTags = [TransformationTags.GameSpeed];

export default function GameTickProcessor(props: {
  setPlayerState: (state: GameState) => void;
  onLoadingProgress: (ticksProcessedSoFar: number, ticksTotal: number) => void;
}) {
  const state = useGameState();

  // The use of ref is needed here because:
  // 1. We don't want to reset the interval if the context changes,
  //    because that will lead to delayed updates
  // 2. If we don't use playerContextRef and use playerContext instead,
  //    it will refer to an old context,
  //    and it will miss updates
  const stateRef = useRef(state);
  stateRef.current = state;

  const [currentError, setCurrentError] = useState(null);

  const [lastTickTime, setLastTickTime] = useState(state.run.lastProcessedTime);

  const [randomValueForReprocessing, setRandomValueForReprocessing] = useState(
    Math.random(),
  );

  const [warping, setWarping] = useState(false);

  if (currentError != null) {
    // If we have an error, stop all processing by throwing
    // and letting the error handler handle it
    throw currentError;
  }

  const timeTickCallback = useCallback(async () => {
    let nowTime = Date.now();
    let lastTickTimeConsidered = lastTickTime;

    if (warping) {
      // Skip ticks entirely if warping
      return;
    }

    if (stateRef.current.run.warpTimeSecs > 0) {
      // Trigger a time warp!
      let { apply, ...currentState } = stateRef.current;
      setWarping(true);
      await processWarpTime(
        currentState,
        () => Date.now(),
        props.onLoadingProgress,
        props.setPlayerState,
      );
      // Processing can take a while, so overwrite this so we don't end up with a long tick...
      nowTime = Date.now();
      setWarping(false);
      lastTickTimeConsidered = nowTime;
    }

    const elapsedSec =
      (nowTime - lastTickTimeConsidered) / 1000 +
      stateRef.current.run.skipAheadTime;
    if (stateRef.current.run.skipAheadTime > 0) {
      stateRef.current.apply((state) => {
        state.run.skipAheadTime = 0;
        return state;
      });
    }
    // console.log("elapsed sec: ", elapsedSec, new Date().toTimeString());
    if (elapsedSec <= 0) {
      // Someone is skipping time. Do not do anything!!
      // Or maybe the system clock adjusted itself. In which case, although we do not
      // want to do anything, we do want the time tick to resume later, so
      // just set a random value so it refreshes...
      setRandomValueForReprocessing(Math.random());
      return;
    }
    if (elapsedSec > 600) {
      // console.warn("elapsed sec is too large", elapsedSec);
      // Trigger a loading update
      const secsTotal = Math.floor(Math.min(60 * 60 * 48, elapsedSec));
      let { apply, ...currentState } = stateRef.current;
      await processOfflineTicks(
        currentState,
        secsTotal,
        nowTime,
        props.setPlayerState,
      );
    } else if (elapsedSec > 30) {
      // Start a warp!
      stateRef.current.apply((state) => {
        state.run.warpTimeSecs += elapsedSec;
        state.run.lastProcessedTime = nowTime;
        return state;
      });
    } else {
      stateRef.current.apply((state) => {
        const beginTimestamp = Date.now();
        let remainingSec = elapsedSec;
        let ticksRun = 0;
        const speedMultiplier = applyTransformations(
          gameSpeedTags,
          state,
          1.0,
          {
            isWarp: false,
          },
        );
        do {
          // Attempt to do a tick once every 100 in-game milliseconds at most.
          // However, if we would need to execute more than 20 ticks, just do 20 ticks to avoid locking the game.
          const tickTime = Math.min(
            Math.max(0.1 / speedMultiplier, elapsedSec / 19.999),
            remainingSec,
          );
          state = processTimeTick(tickTime, false)(state);
          state.run.lastProcessedTime = nowTime;
          remainingSec -= tickTime;
          ticksRun++;
        } while (remainingSec > 0);
        const endTimestamp = Date.now();
        state.global.debugLastUpdateTimestamps.push(endTimestamp);
        state.global.debugLastUpdateBeginEndTimestamps.push({
          begin: beginTimestamp,
          end: endTimestamp,
          ticksRun,
        });
        state.global.debugLastUpdateTimestamps =
          state.global.debugLastUpdateTimestamps.slice(-10);
        state.global.debugLastUpdateBeginEndTimestamps =
          state.global.debugLastUpdateBeginEndTimestamps.slice(-10);
        return state;
      });
    }

    setLastTickTime(nowTime);
  }, [lastTickTime, warping, randomValueForReprocessing]);

  const cleanupCallback = useCallback(async () => {
    stateRef.current.apply(cleanup);
  }, []);

  useEffect(() => {
    const interval = setTimeout(async () => {
      try {
        await timeTickCallback();
        await nextFrame();
      } catch (e: any) {
        setCurrentError(e);
      }
    }, 1000 / (state.options?.targetFps ?? TARGET_FPS));
    return () => clearTimeout(interval);
  }, [timeTickCallback]);

  useEffect(() => {
    const interval = setInterval(async () => {
      try {
        await cleanupCallback();
        await nextFrame();
      } catch (e: any) {
        setCurrentError(e);
      }
    }, 3000);
    return () => clearTimeout(interval);
  }, [cleanupCallback]);

  // Do not render anything
  return null;
}
