import { hasFlag, hasWorldFlag, setFlag, setWorldFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { DataStore } from "../../generic/DataStore";
import { getMaxOfMaxPrimaryElementLevels } from "../../spells/Elements";
import { Dungeon } from "./Dungeon";
import { DungeonFloor } from "./DungeonFloor";

const Dungeons = new DataStore<Dungeon>();
const DungeonFloors = new DataStore<DungeonFloor>();

export { Dungeons, DungeonFloors };

export function getUnlockedDungeons(state: GameState): Dungeon[] {
  return Dungeons.getAll().filter((dungeon) => dungeon.isUnlocked(state));
}

export function isDungeonFloorCompletedThisRun(
  state: GameState,
  floor: DungeonFloor | string,
): boolean {
  return hasFlag(
    state,
    (typeof floor === "string" ? floor : floor.getId()) +
      "_dungeon_floor_completed",
  );
}

export function isDungeonFloorEverCompleted(
  state: GameState,
  floor: DungeonFloor | string,
): boolean {
  return hasWorldFlag(
    state,
    (typeof floor === "string" ? floor : floor.getId()) +
      "_dungeon_floor_completed",
  );
}

export function setDungeonFloorCompleted(
  state: GameState,
  floorId: string,
): GameState {
  state = setFlag(floorId + "_dungeon_floor_completed")(state);
  state.run.lastDungeonFloorCompleted = floorId;
  return setWorldFlag(floorId + "_dungeon_floor_completed")(state);
}

export function minMPLRecommendation(state: GameState): number {
  const unlockedDungeons = getUnlockedDungeons(state);
  const floors = unlockedDungeons
    .map((dungeon) =>
      dungeon
        .getDungeonFloors()
        .filter(
          (floor) => floor.isUnlocked(state) && floor.isMainStoryDungeonFloor(),
        ),
    )
    .flat();
  const maxLowRecommendation = Math.max(
    ...floors.map((floor) => floor.getMinimumMPLRecommendation() ?? 0),
  );
  return maxLowRecommendation;
}

export function mplIsTooLow(state: GameState): boolean {
  const maxLowRecommendation = minMPLRecommendation(state);
  const currentMPL = getMaxOfMaxPrimaryElementLevels(state);
  return currentMPL < maxLowRecommendation;
}

export function maxMPLRecommendation(state: GameState): number {
  const unlockedDungeons = getUnlockedDungeons(state);
  const floors = unlockedDungeons
    .map((dungeon) =>
      dungeon
        .getDungeonFloors()
        .filter(
          (floor) => floor.isUnlocked(state) && floor.isMainStoryDungeonFloor(),
        ),
    )
    .flat();
  const maxHighRecommendation = Math.max(
    ...floors.map((floor) => floor.getMaximumMPLRecommendation() ?? Infinity),
  );
  return maxHighRecommendation;
}

export function mplIsTooHigh(state: GameState): boolean {
  const minHighRecommendation = maxMPLRecommendation(state);
  const currentMPL = getMaxOfMaxPrimaryElementLevels(state);
  return currentMPL > minHighRecommendation;
}
