import { formatNumber } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import {
  AutocastConditionParameterDisplay,
  AutocastConditionParameterOption,
  AutocastConditionParameterType,
} from "../../AutocastConditionParameterType";

export class NumericAutocastConditionParameterType extends AutocastConditionParameterType {
  constructor(
    private min: number | undefined,
    private max: number | undefined,
  ) {
    super();
  }

  getDisplay(): AutocastConditionParameterDisplay {
    return AutocastConditionParameterDisplay.TEXT_FIELD;
  }

  getOptions(state: GameState): AutocastConditionParameterOption[] {
    return [];
  }

  validate(state: GameState, value: string): string | undefined {
    try {
      const amount = Number.parseFloat(value);
      if (Number.isNaN(amount)) {
        return t("Value must be a number");
      }
      if (this.min != undefined && amount < this.min) {
        return t("Value must be {{min}} or higher", {
          min: formatNumber(this.min),
        });
      }
      if (this.max != undefined && amount > this.max) {
        return t("Value must be {{max}} or lower", {
          max: formatNumber(this.max),
        });
      }
    } catch (e) {
      return t("Value must be a number");
    }
    return;
  }
}
