import moment from "moment";
import { t } from "./I18n";

export enum RoundingMethod {
  DEFAULT = "DEFAULT",
  UP = "UP",
  DOWN = "DOWN",
}

const MAX_FORMAT_VALUE = Number.MAX_VALUE / 100;

export function formatNumber(
  num: number,
  options: { showDecimals: boolean; roundingMethod?: RoundingMethod } = {
    showDecimals: false,
    roundingMethod: undefined,
  },
): string {
  num = Number(num);
  if (num >= MAX_FORMAT_VALUE) {
    return "";
  }
  if (num >= 1e30) {
    return num.toExponential(2);
  }
  if (num >= 1e27) {
    return (
      formatNumber(num / 1e27, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Oc"
    );
  }
  if (num >= 1e24) {
    return (
      formatNumber(num / 1e24, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Sp"
    );
  }
  if (num >= 1e21) {
    return (
      formatNumber(num / 1e21, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Sx"
    );
  }
  if (num >= 1e18) {
    return (
      formatNumber(num / 1e18, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Qi"
    );
  }
  if (num >= 1e15) {
    return (
      formatNumber(num / 1e15, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "Qa"
    );
  }
  if (num >= 1000000000000) {
    return (
      formatNumber(num / 1000000000000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "T"
    );
  }
  if (num >= 1000000000) {
    return (
      formatNumber(num / 1000000000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "B"
    );
  }
  if (num >= 1000000) {
    return (
      formatNumber(num / 1000000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "M"
    );
  }
  if (num >= 10000) {
    return (
      formatNumber(num / 1000, {
        showDecimals: true,
        roundingMethod: options.roundingMethod,
      }) + "K"
    );
  }

  const roundingMethod = options.roundingMethod ?? RoundingMethod.DEFAULT;

  if (options.showDecimals && Math.abs(num) < 10 && !Number.isInteger(num)) {
    const base =
      roundingMethod == RoundingMethod.DEFAULT
        ? Math.round(num * 100)
        : roundingMethod != RoundingMethod.UP
        ? Math.floor(num * 100)
        : Math.ceil(num * 100);
    return (base / 100.0).toFixed(2);
  } else if (
    options.showDecimals &&
    Math.abs(num) < 100 &&
    !Number.isInteger(num)
  ) {
    const base =
      roundingMethod == RoundingMethod.DEFAULT
        ? Math.round(num * 10)
        : roundingMethod != RoundingMethod.UP
        ? Math.floor(num * 10)
        : Math.ceil(num * 10);
    return (Math.floor(base) / 10.0).toFixed(1);
  } else {
    const base =
      roundingMethod == RoundingMethod.DEFAULT
        ? Math.round(num)
        : roundingMethod != RoundingMethod.UP
        ? Math.floor(num)
        : Math.ceil(num);
    return base.toFixed();
  }
}

export function formatMultiplier(
  ratio: number,
  options: { showDecimals: boolean; roundingMethod?: RoundingMethod } = {
    showDecimals: false,
    roundingMethod: RoundingMethod.DEFAULT,
  },
): string {
  const pct = (ratio - 1.0) * 100;
  if (pct > 0) {
    if (pct > 999.5) {
      return t("{{ratio}}x", {
        ratio: formatNumber(ratio, { showDecimals: true }),
      });
    }
    return t("+{{pct}}%", { pct: formatNumber(pct, options) });
  } else {
    return t("{{pct}}%", { pct: formatNumber(pct, options) });
  }
}

export function formatTime(
  secs: number,
  options: { shortForm: boolean; showDecimals: boolean } = {
    shortForm: false,
    showDecimals: false,
  },
): string {
  const { shortForm, showDecimals } = options;
  let unit = shortForm ? t("sec") : t("second(s)");
  let time = formatNumber(secs, { showDecimals: showDecimals });
  if (secs > 60) {
    unit = shortForm ? t("min") : t("minute(s)");
    time = formatNumber(secs / 60, { showDecimals: showDecimals });
  }
  if (secs > 60 * 60) {
    unit = shortForm ? t("hr") : t("hour(s)");
    time = formatNumber(secs / (60 * 60), { showDecimals: showDecimals });
  }
  return t("{{time}} {{unit}}", { time, unit });
}

export function formatTimeLong(duration: moment.Duration) {
  if (duration.asDays() < 1.0) {
    return t("{{hours}}:{{minutes}}:{{seconds}}", {
      hours: duration.hours().toFixed().padStart(2, "0"),
      minutes: duration.minutes().toFixed().padStart(2, "0"),
      seconds: duration.seconds().toFixed().padStart(2, "0"),
    });
  }

  return t("{{days}} days, {{hours}}:{{minutes}}:{{seconds}}", {
    hours: duration.hours().toFixed().padStart(2, "0"),
    minutes: duration.minutes().toFixed().padStart(2, "0"),
    seconds: duration.seconds().toFixed().padStart(2, "0"),
    days: formatNumber(Math.floor(duration.asDays())),
  });
}
