import * as React from "react";
import {
  Dimensions,
  LayoutChangeEvent,
  Platform,
  Pressable,
  StyleSheet,
  View,
  ViewStyle,
  useWindowDimensions,
} from "react-native";

import type { ThemeProp } from "react-native-paper/lib/typescript/src/types";

import { Portal, Surface, useTheme } from "react-native-paper";
import { Markdown } from "../Markdown";
import Styles from "../Styles";
import { VisibilityContext } from "../VisibilityContext";
import { addEventListener } from "./addEventListener";
import { Measurement, getTooltipPosition } from "./utils";

export type Props = {
  /**
   * Tooltip reference element. Needs to be able to hold a ref.
   */
  children: React.ReactElement;
  /**
   * The number of milliseconds a user must touch the element before showing the tooltip.
   */
  enterTouchDelay?: number;
  /**
   * The number of milliseconds after the user stops touching an element before hiding the tooltip.
   */
  leaveTouchDelay?: number;
  /**
   * Tooltip title
   */
  title: string;
  /**
   * @optional
   */
  theme?: ThemeProp;
  hitSlop?: any;
};

/**
 * Tooltips display informative text when users hover over, focus on, or tap an element.
 *
 * Plain tooltips, when activated, display a text label identifying an element, such as a description of its function. Tooltips should include only short, descriptive text and avoid restating visible UI text.
 *
 * <div class="screenshots">
 *   <figure>
 *     <img class="small" src="screenshots/tooltip.png" />
 *   </figure>
 * </div>
 *
 * ## Usage
 * ```js
 * import * as React from 'react';
 * import { IconButton, Tooltip } from 'react-native-paper';
 *
 * const MyComponent = () => (
 *   <Tooltip title="Selected Camera">
 *     <IconButton icon="camera" selected size={24} onPress={() => {}} />
 *   </Tooltip>
 * );
 *
 * export default MyComponent;
 * ```
 */
const Tooltip = ({ children, title, hitSlop, ...rest }: Props) => {
  const enterTouchDelay = 0;
  const theme = useTheme();
  const [visible, setVisible] = React.useState(false);

  const [measurement, setMeasurement] = React.useState({
    children: {},
    tooltip: {},
    measured: false,
  });
  const showTooltipTimer = React.useRef<NodeJS.Timeout>();
  const hideTooltipTimer = React.useRef<NodeJS.Timeout>();
  const childrenWrapperRef = React.useRef() as React.MutableRefObject<View>;
  const touched = React.useRef(false);

  const isWeb = Platform.OS === "web";

  React.useEffect(() => {
    return () => {
      if (showTooltipTimer.current) {
        clearTimeout(showTooltipTimer.current);
      }

      if (hideTooltipTimer.current) {
        clearTimeout(hideTooltipTimer.current);
      }
    };
  }, []);

  React.useEffect(() => {
    const subscription = addEventListener(Dimensions, "change", () =>
      setVisible(false),
    );

    return () => subscription.remove();
  }, []);

  const handleOnLayout = ({ nativeEvent: { layout } }: LayoutChangeEvent) => {
    childrenWrapperRef.current.measure(
      (_x, _y, width, height, pageX, pageY) => {
        setMeasurement({
          children: { pageX, pageY, height, width },
          tooltip: { ...layout },
          measured: true,
        });
      },
    );
  };

  const handleTouchStart = () => {
    if (hideTooltipTimer.current) {
      clearTimeout(hideTooltipTimer.current);
    }

    showTooltipTimer.current = setTimeout(() => {
      touched.current = true;
      setVisible(true);
    }, enterTouchDelay) as unknown as NodeJS.Timeout;
  };

  const handleTouchEnd = () => {
    touched.current = false;
    if (showTooltipTimer.current) {
      clearTimeout(showTooltipTimer.current);
    }

    // hideTooltipTimer.current = setTimeout(() => {
    //   setVisible(false);
    //   setMeasurement({ children: {}, tooltip: {}, measured: false });
    // }, leaveTouchDelay) as unknown as NodeJS.Timeout;
  };

  const hide = React.useCallback(() => {
    setVisible(false);
    setMeasurement({ children: {}, tooltip: {}, measured: false });
  }, []);

  const mobilePressProps = {
    onPress: React.useCallback(() => {
      if (touched.current) {
        return null;
      } else {
        return children.props.onPress?.();
      }
    }, [children.props]),
    onPressIn: () => handleTouchStart(),
    onPressOut: () => handleTouchEnd(),
  };

  const webPressProps = {
    onHoverIn: () => {
      handleTouchStart();
      children.props.onHoverIn?.();
    },
    onHoverOut: () => {
      handleTouchEnd();
      hide();
      children.props.onHoverOut?.();
    },
  };

  const isTextVisible = React.useContext(VisibilityContext);

  const { height, width } = useWindowDimensions();

  const innerSurface = (
    <Surface
      onLayout={handleOnLayout}
      style={[
        styles.tooltip,
        {
          ...getTooltipPosition(measurement as Measurement),
          borderRadius: theme.roundness,
          ...(measurement.measured ? styles.visible : styles.hidden),
        },
      ]}
      testID="tooltip-container"
    >
      <View style={Styles.flexCenter}>
        <Markdown
          accessibilityLiveRegion="polite"
          selectable={false}
          style={{
            color: theme.colors.onSurface,
          }}
        >
          {title}
        </Markdown>
      </View>
    </Surface>
  );

  return (
    <>
      {visible && (
        <Portal>
          {Platform.OS != "web" && (
            <Pressable onPressIn={hide}>
              <View style={[styles.fullView, { height, width }]} />
            </Pressable>
          )}
          {Platform.OS != "web" ? (
            <Pressable onPressIn={hide}>{innerSurface}</Pressable>
          ) : (
            innerSurface
          )}
        </Portal>
      )}
      {/* Need the xxPressProps in both places */}
      <Pressable
        ref={childrenWrapperRef}
        style={[styles.pressContainer]}
        hitSlop={hitSlop}
        {...(isWeb ? webPressProps : mobilePressProps)}
      >
        <View style={!isTextVisible && styles.textInvisible}>
          {React.cloneElement(children, {
            ...rest,
            ...(isWeb ? webPressProps : mobilePressProps),
          })}
        </View>
      </Pressable>
    </>
  );
};

const styles = StyleSheet.create({
  tooltip: {
    alignSelf: "flex-start",
    justifyContent: "center",
    paddingHorizontal: 16,
    maxWidth: 300,
    paddingVertical: 8,
  },
  visible: {
    opacity: 1,
  },
  hidden: {
    opacity: 0,
  },
  fullView: {
    position: "absolute",
    left: 0,
    top: 0,
    backgroundColor: "rgba(0, 0, 0, 0)",
  },
  pressContainer: {
    cursor: "default",
    alignSelf: "flex-start",
  } as ViewStyle,
  textInvisible: {
    display: "none",
  },
});

export default Tooltip;
