import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { SpellElement } from "../../../spells/Elements";
import { Chilled } from "../../../temporaryeffects/Chilled";
import { poison } from "../../../temporaryeffects/Poison";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { Wet } from "../../../temporaryeffects/Wet";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  getEnemyTurnCounter,
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { pushCombatActionResult } from "../../ExplorationActionResult";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class MutatedFish extends Enemy {
  getId(): string {
    return "mutatedFish";
  }

  getName(): string {
    return t("Mutated Fish");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 3000,
      attack: 40,
      defense: 30,
      attackDelay: 2.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./mutatedFish.png");
  }

  isBoss(): boolean {
    return true;
  }

  getLevel(): number {
    return 13;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 99;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 60;
  }

  getNextAction(state: GameState): BattlerAction {
    if (getEnemyTurnCounter(state) % 12 == 0) {
      return {
        name: t("Tsunami"),
        delay: this.getAttackDelay(state) * 3,
        transform: (state) => {
          state = grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Enemy,
          })(state);
          state = grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Player,
          })(state);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 3, {
            tags: [SpellElement.Water],
          });
          state = modifyEnemyCurrentHP(state, -300);
          state = pushCombatActionResult(state, {
            id: Math.random().toString(),
            damage: 300,
            isCrit: false,
            isHit: true,
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
            time: getSecondsPlayed(state),
          });
          return state;
        },
      };
    }
    if (
      (!hasTemporaryEffect(state, Wet.getId(), AttackTarget.Enemy) &&
        !hasTemporaryEffect(state, Chilled.getId(), AttackTarget.Enemy)) ||
      Math.random() < 0.5
    ) {
      return {
        name: t("Ripple"),
        delay: this.getAttackDelay(state),
        transform: (state) => {
          state = grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Enemy,
          })(state);
          state = grantTemporaryEffect(Wet.getId(), 60, {
            target: AttackTarget.Player,
          })(state);
          state = standardEnemyAttackEffect(state, this.getAttack(state), {
            tags: [SpellElement.Water],
          });
          state = modifyEnemyCurrentHP(state, -30);
          state = pushCombatActionResult(state, {
            id: Math.random().toString(),
            damage: 30,
            isCrit: false,
            isHit: true,
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
            time: getSecondsPlayed(state),
          });
          return state;
        },
      };
    }
    if (
      Math.random() < 0.05 &&
      !hasTemporaryEffect(state, poison.getId(), AttackTarget.Player)
    ) {
      return {
        name: t("Poisonous Bite"),
        delay: this.getAttackDelay(state),
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.6);
          state = grantTemporaryEffect(poison.getId(), 15, {
            target: AttackTarget.Player,
            params: {
              magnitude: this.getAttack(state) / 20,
            },
          })(state);
          return state;
        },
      };
    }
    if (Math.random() < 0.4) {
      return {
        name: t("Fast Attack"),
        delay: this.getAttackDelay(state) * 0.5,
        transform: (state) =>
          standardEnemyAttackEffect(state, this.getAttack(state) * 0.8),
      };
    }
    return super.getNextAction(state);
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: copperOre.getId(),
        amount: 4,
        chance: 1.0,
      },
    ];
  }

  getTips(): string {
    return t(
      `This might have used to be a Sewer Fish, but something odd has happened to it! It is strong. It will use Water attacks that also hit itself. This will make them Wet, which will reduce the effectivity of your Fire attacks. It also seems to have a really strong Tsunami attack... watch out! The Guard skill might come in handy, if your Earth is Lv12 or higher.`,
    );
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "sewerEntrance");
    return state;
  }
}

export default new MutatedFish();
