import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { buildEvent } from "../events/EventMessageBuilder";
import {
  EventOccurrenceData,
  getEventOccurrenceById,
  triggerEvent,
} from "../events/Events";
import { clearFlag, hasFlag, hasWorldFlag, setWorldFlag } from "../Flags";
import { GameState } from "../GameState";
import { getResourceAmount, grantResource, Resource } from "../Resources";
import {
  registerPostRetirementListener,
  registerRetirementListener,
} from "../Retire";
import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import {
  OfflineTickListener,
  OfflineTickListeners,
} from "./OfflineTickListeners";
import { registerTimeTickListener } from "./TimeTick";

export function getCurrentTimePieceSpeedupSpeed(state: GameState) {
  return hasFlag(state, "time_piece_5x_speedup_enabled")
    ? 5
    : hasFlag(state, "time_piece_speedup_enabled")
    ? 2
    : 1;
}

// Returns cost per extra REAL LIFE second gained.
export function getCurrentTimePieceSpeedupCost(state: GameState) {
  if (hasFlag(state, "time_piece_5x_speedup_enabled")) {
    return applyTransformations(
      [TransformationTags.TimePiecePerFasterSpeedup],
      state,
      4.0,
    );
  } else if (hasFlag(state, "time_piece_speedup_enabled")) {
    return applyTransformations(
      [TransformationTags.TimePiecePerFastSpeedup],
      state,
      3.0,
    );
  }

  return 0;
}

// Returns cost per real-life second.
export function getCurrentTimePieceSpeedupCostPerRealSecond(state: GameState) {
  const speed = getCurrentTimePieceSpeedupSpeed(state);
  if (speed > 1) {
    const cost = getCurrentTimePieceSpeedupCost(state);
    return (speed - 1) * cost;
  }
  return 0;
}

const gameSpeedTags = [TransformationTags.GameSpeed];

/**
 * Returns the total speed multiplier. This includes time pieces,
 * Time Contraction, and everything else.
 */
function calculateSpeedMultiplier(state: GameState): number {
  return applyTransformations(gameSpeedTags, state, 1.0, { isWarp: false });
}

function getCurrentTimePieceSpeedupCostPerGameSecond(state: GameState) {
  const speed = getCurrentTimePieceSpeedupSpeed(state);
  if (speed > 1) {
    const cost = getCurrentTimePieceSpeedupCost(state);
    const totalSpeed = calculateSpeedMultiplier(state);
    return ((speed - 1) * cost) / totalSpeed;
  }
  return 0;
}

export function recordTimePieceConsumption(
  state: GameState,
  amount: number,
): GameState {
  state.global.timePiecesConsumed += amount;
  state.world.timePiecesConsumed += amount;
  state.run.timePiecesConsumed += amount;
  return state;
}

export function loadTimePieces() {
  registerTransformation(
    [[TransformationTags.GameSpeed]],
    "TimePieceGameSpeed",
    t("Time Pieces game speedup"),
    TransformationType.Multiplier,
    (state, params) =>
      !params.isWarp ? getCurrentTimePieceSpeedupSpeed(state) : 1,
  );

  registerTransformation(
    [[TransformationTags.GameSpeed]],
    "Debug2xGameSpeed",
    t("Debug game speedup"),
    TransformationType.Multiplier,
    (state, params) =>
      hasFlag(state, "debug_2x_speed") && !params?.isWarp ? 2 : 1,
  );

  registerTransformation(
    [[TransformationTags.GameSpeed]],
    "Debug0xGameSpeed",
    t("Debug game speed frozen"),
    TransformationType.Multiplier,
    (state, params) =>
      hasFlag(state, "debug_0x_speed") && !params?.isWarp ? 0 : 1,
  );

  registerTimeTickListener(
    "TimePieceConsumer",
    (state, timeElapsedSec, isWarp) => {
      if (!isWarp) {
        const costPerSecond =
          getCurrentTimePieceSpeedupCostPerGameSecond(state);
        if (costPerSecond > 0) {
          const amount = costPerSecond * timeElapsedSec;
          state = grantResource(Resource.TimePiece, -amount)(state);
          state = recordTimePieceConsumption(state, amount);
        }
        if (getResourceAmount(state, Resource.TimePiece) <= 0) {
          state = clearFlag("time_piece_5x_speedup_enabled")(state);
          state = clearFlag("time_piece_speedup_enabled")(state);
        }
      }
      return state;
    },
  );

  const timePieceEventBuilder = buildEvent(
    "TimePieceEvent",
    t("Offline Progress"),
    [],
  );
  timePieceEventBuilder.message(
    t(`Welcome back to Magic Research!
        
  **You obtained /amount/:timepiece: Time Pieces!**`),
  );
  const timePieceEvent = timePieceEventBuilder.build();

  const timePieceEventBuilderFirstTime = buildEvent(
    "TimePieceEventFirstTime",
    t("Offline Progress"),
    [],
  ).setOnTrigger(setWorldFlag("has_seen_offline_progress"));
  timePieceEventBuilderFirstTime.message(
    t(`Welcome back to Magic Research!
        
  **You obtained /amount/:timepiece: Time Pieces! Time Pieces allow you to make time pass faster for a duration. You can do so from the left side menu!**`),
  );
  const timePieceEventFirstTime = timePieceEventBuilderFirstTime.build();

  const timePieceGranter = new OfflineTickListener(
    "TimePieceGranter",
    (totalSecs) => {
      return (state) => {
        const transformedSecs = applyTransformations(
          [TransformationTags.OfflineTimePieceGain, Resource.TimePiece],
          state,
          totalSecs,
        );
        const oldTimePiece = getResourceAmount(state, Resource.TimePiece);
        // console.log("Granting time pieces", transformedSecs);
        state = grantResource(Resource.TimePiece, transformedSecs)(state);
        const newTimePiece = getResourceAmount(state, Resource.TimePiece);
        const amount = newTimePiece - oldTimePiece;
        if (amount > 0) {
          if (hasWorldFlag(state, "has_seen_offline_progress")) {
            const lastEventOccurrenceId = state.run.eventQueue.find(
              (eventOccurrenceId) =>
                getEventOccurrenceById(state, eventOccurrenceId)?.eventId ===
                timePieceEvent.getId(),
            );
            if (lastEventOccurrenceId != null) {
              const eventOccurrence = getEventOccurrenceById(
                state,
                lastEventOccurrenceId,
              ) as EventOccurrenceData;
              const newAmount =
                amount + (eventOccurrence?.params?.rawAmount ?? 0);
              eventOccurrence.params.amount = formatNumber(newAmount);
              eventOccurrence.params.rawAmount = newAmount;
              state.run.eventOccurrences[lastEventOccurrenceId] =
                eventOccurrence;
            } else {
              if (amount > 60) {
                state = triggerEvent(timePieceEvent, {
                  amount: formatNumber(amount),
                  rawAmount: amount,
                })(state);
              }
            }
          } else {
            state = triggerEvent(timePieceEventFirstTime, {
              amount: formatNumber(amount),
              rawAmount: amount,
            })(state);
          }
        }
        return state;
      };
    },
  );
  OfflineTickListeners.register(timePieceGranter);

  registerRetirementListener("TimePieceRecorder", (state) => {
    state.world.variables["timePiecesLastRun"] = getResourceAmount(
      state,
      Resource.TimePiece,
    );
    return state;
  });

  registerPostRetirementListener("TimePieceRetirementGranter", (state) => {
    state = grantResource(
      Resource.TimePiece,
      state.world.variables["timePiecesLastRun"] as number,
    )(state);
    return state;
  });
}
