import { Achievement } from "../backend/Achievement";
import { electron, isElectron } from "./ElectronUtils";

export const ACHIEVEMENTS_SUPPORTED = isElectron();

export function unlockAchievementRaw(achievementName: Achievement): void {
  // On Electron, we do want to do something!
  if (isElectron()) {
    electron().ipcRenderer.send("unlockAchievement", achievementName);
  }
}
