import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { manaSteamer } from "../../buildings/mana/ManaSteamer";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { clearCalculatedIncomeCache } from "../../timetick/IncomeOverTime";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";
import { manaSteamers } from "../other/ManaSteamers";

class SuperheatedSteamers extends Enhancement {
  getId(): string {
    return "superheatedSteamers";
  }
  getBaseName(): string {
    return t("Superheated Steamers");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_PRODUCTION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 20000,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 30,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [manaSteamers];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Enhance your Mana Steamers so they produce and consume much faster than before.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t(
      "+50% :mana: production; 4x :fireessence: and :wateressence: consumption of Mana Steamers",
    );
  }
}

const superheatedSteamers = new SuperheatedSteamers();

registerTransformation(
  [[manaSteamer.getId(), TransformationTags.Production]],
  superheatedSteamers.getId(),
  superheatedSteamers.getBaseName(),
  TransformationType.Multiplier,
  (state) => (superheatedSteamers.isPurchased(state) ? 1.5 : 1.0),
);

registerTransformation(
  [[manaSteamer.getId(), TransformationTags.Consumption]],
  superheatedSteamers.getId() + "consumption",
  superheatedSteamers.getBaseName(),
  TransformationType.Multiplier,
  (state) => (superheatedSteamers.isPurchased(state) ? 4.0 : 1.0),
);

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == superheatedSteamers) {
    state = clearCalculatedIncomeCache(state);
  }
  return state;
});

export { superheatedSteamers };
