import { useCallback, useMemo, useRef, useState } from "react";
import { ItemAction } from "../../backend/items/Item";
import { t } from "../../utils/I18n";
import { ActiveMark } from "../utility/ActiveMark";
import { Button } from "../utility/Button";
import { Markdown } from "../utility/Markdown";
import Styles from "../utility/Styles";
import { useGameState } from "../utility/useGameState";

export function ItemDetailsAction(props: {
  action: ItemAction;
  extraOnPress?: () => void;
}) {
  const action = props.action;
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const [pressedOnce, setPressedOnce] = useState(false);
  const onPressCallback = useCallback(() => {
    if (action.requireConfirm && !pressedOnce) {
      setPressedOnce(true);
      return;
    }
    action.transform && stateRef.current.apply(action.transform);
    props.extraOnPress && props.extraOnPress();
  }, [
    action.requireConfirm,
    action.requireConfirm && setPressedOnce,
    action.requireConfirm && pressedOnce,
    action.transform,
    props.extraOnPress,
  ]);
  return useMemo(() => {
    return (
      <Button
        key={action.id}
        icon={
          action.isActive
            ? () => <ActiveMark disabled={!action.isEnabled} />
            : undefined
        }
        disabled={!action.isEnabled}
        style={Styles.mr8}
        enableAutoRepress={true}
        autoRepressConfig={{
          dropoffMultiplier: 0.8,
          startingThreshold: 300,
          startingRepressTime: 200,
          minTimeMs: 50,
        }}
        compact={true}
        onPress={onPressCallback}
        TextComponent={Markdown}
      >
        {action.requireConfirm && pressedOnce
          ? t("Press again to confirm")
          : action.description}
      </Button>
    );
  }, [
    action.id,
    action.description,
    action.isEnabled,
    action.isActive,
    action.requireConfirm,
    action.requireConfirm && pressedOnce,
    onPressCallback,
  ]);
}
