import { t } from "../../../../utils/I18n";
import { Flag } from "../../../Flag";
import { hasFlag } from "../../../Flags";
import { GameState } from "../../../GameState";
import { Resource } from "../../../Resources";
import { SpellElement } from "../../../spells/Elements";
import { Item, ItemParams } from "../../Item";
import { ItemTagEnum } from "../../ItemTagEnum";
import { TransmutationSpell } from "../TransmutationSpell";

class MonstiumPaper extends Item {
  getId(): string {
    return "monstiumPaper";
  }

  getTags(): ItemTagEnum[] {
    return [ItemTagEnum.Material];
  }

  getPicture() {
    return require("./monstiumPaper.png");
  }

  getElement(): SpellElement {
    return SpellElement.Mind;
  }

  getBaseName(params: ItemParams): string {
    return t("Monstium Paper");
  }

  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t("Monstium Paper. Used for stronger Enhancements.");
  }

  getEffect(state: GameState, params: ItemParams): string | undefined {
    return undefined;
  }

  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 2000;
  }
}

export const monstiumPaper = new MonstiumPaper();

class TransmuteMonstiumPaper extends TransmutationSpell {
  getItem(): Item {
    return monstiumPaper;
  }

  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Mind: 1,
    };
  }

  canCraft(state: GameState): boolean {
    return hasFlag(state, Flag.DeepCavernsUnlocked) && super.canCraft(state);
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.DeepCavernsUnlocked) && super.isVisible(state);
  }

  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 800,
        Monstium: 4000,
        MindEssence: 25000,
      },
      items: {
        paper: 1,
      },
    };
  }

  getElement(): SpellElement {
    return SpellElement.Mind;
  }
}

export const transmuteMonstiumPaper = new TransmuteMonstiumPaper();
