import { t } from "../../../../../utils/I18n";
import { GameState } from "../../../../GameState";
import { Resource, getResourceAmount } from "../../../../Resources";
import { CombatStat } from "../../../../exploration/CombatStats";
import { SpellElement } from "../../../../spells/Elements";
import {
  TransformationType,
  registerTransformation,
} from "../../../../transformation/Transformation";
import { EquipmentSlot } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class RedConflagration extends EquippableItem {
  getId(): string {
    return "redConflagration";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Hand;
  }
  getExtraSlots(): EquipmentSlot[] {
    return [EquipmentSlot.Accessory];
  }
  getPicture() {
    return require("./redConflagration.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Red Conflagration");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A massive two-handed sword. Leverages :fireessence: to empower itself further, but spellcasting suffers.",
    );
  }
  getEffectExtra(state: GameState, params: ItemParams): string {
    return t("Extra large");
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 9000;
  }

  getMaxManaBonusBase(params: ItemParams): number {
    return -2000;
  }

  getAttackBonusBase(params: ItemParams): number {
    return 340;
  }

  getAttackDelayBonusRatioBase(params: ItemParams): number {
    return 0.4;
  }
}

export const redConflagration = new RedConflagration();

registerTransformation(
  [[redConflagration.getId(), CombatStat.Attack]],
  redConflagration.getId() + "fireEssence",
  t(":fireessence: amount"),
  TransformationType.Multiplier,
  (state) => {
    const fireEssence = getResourceAmount(state, Resource.FireEssence);
    return Math.max(
      0.5,
      Math.floor(Math.log10(fireEssence / 15000.0) * 10) / 10,
    );
  },
);

class TransmuteRedConflagration extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return redConflagration;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Fire: 27,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 700,
        FireEssence: 120000,
      },
      items: {
        elementalPrismFire: 30,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Fire;
  }
}

export const transmuteRedConflagration = new TransmuteRedConflagration();
