import { t } from "../../../../utils/I18n";
import { sewers } from "../../../exploration/dungeons/sewers/Sewers";
import { getCurrentDungeonFloor } from "../../../exploration/Exploration";
import { ExplorationStatus } from "../../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../../exploration/ExplorationUtils";
import { Flag } from "../../../Flag";
import { hasFlag, setFlag } from "../../../Flags";
import { addGameStateListener } from "../../../GameStateListeners";
import { wizardsHat } from "../../../items/equipment/accessory/water/WizardsHat";
import { addToInventory } from "../../../items/Inventory";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { registerPostRetirementListener } from "../../../Retire";
import { getElementLevel, SpellElement } from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { registerRandomEventTrigger } from "../../autorandom/RandomEventTrigger";
import { buildEvent } from "../../EventMessageBuilder";
import { getGameEventById } from "../../Events";
import { EventTag } from "../../GameEvent";

const someonesTrashBookStoryline = new Storyline(
  "someonesTrashBook",
  t("Somebody's Trash (Book)"),
  250,
  20,
  (state) =>
    getGameEventById("someonesTrash").hasTriggered(state)
      ? !someonesTrashHatStoryline.isCompletedThisRun(state) &&
        !someonesTrashTreasureStoryline.isCompletedThisRun(state)
        ? t("Explore the Sewers")
        : t("Explore the Sewers, but you cannot get it this run")
      : t("Explore the Sewers"),
  (state) => t("+25% Exp gain from Studying"),
  (_state) => true,
);

const someonesTrashHatStoryline = new Storyline(
  "someonesTrashHat",
  t("Somebody's Trash (Hat)"),
  251,
  20,
  (state) =>
    getGameEventById("someonesTrash").hasTriggered(state)
      ? !someonesTrashBookStoryline.isCompletedThisRun(state) &&
        !someonesTrashTreasureStoryline.isCompletedThisRun(state)
        ? t("Explore the Sewers")
        : t("Explore the Sewers, but you cannot get it this run")
      : t("Explore the Sewers"),
  t("Start runs with a Wizard's Hat accessory"),
  (_state) => true,
);

const someonesTrashTreasureStoryline = new Storyline(
  "someonesTrashTreasure",
  t("Somebody's Trash (Treasure Chest)"),
  252,
  20,
  (state) =>
    getGameEventById("someonesTrash").hasTriggered(state)
      ? !someonesTrashBookStoryline.isCompletedThisRun(state) &&
        !someonesTrashHatStoryline.isCompletedThisRun(state)
        ? t("Explore the Sewers")
        : t("Explore the Sewers, but you cannot get it this run")
      : t("Explore the Sewers"),
  t("Start with your :coins: storage filled up"),
  (_state) => true,
);

export function loadSomeonesTrash() {
  Storylines.register(someonesTrashBookStoryline);
  Storylines.register(someonesTrashHatStoryline);
  Storylines.register(someonesTrashTreasureStoryline);

  const someonesTrashEventBuilder = buildEvent(
    "someonesTrash",
    t("(Storyline) Somebody's Trash"),
    [EventTag.STORYLINE],
  ).setStorylineIds([
    someonesTrashBookStoryline.getId(),
    someonesTrashHatStoryline.getId(),
    someonesTrashTreasureStoryline.getId(),
  ]);

  someonesTrashEventBuilder.message(
    t(`On your exploration of the Sewers, you notice your hired wizard falling back, staring at the canal on the tunnel you are traversing. You call to her to come, but she instead tells *you* to do so instead.
    
"Look", she exclaims. "There are a lot of magical items here!"

You are roughly able to see something at the bottom. Except, it looks like a pile of trash, stuck side-by-side on the canal.`),
  );
  someonesTrashEventBuilder.message(
    t(
      `You tell her that it looks worthless, but she shakes her head. "No, you are mistaken", she says. "There's so many valuable things here. Look at that book, for example - it is a Magical Encyclopedia. I can't tell exactly what the contents are, though. Or that hat - it's actually a traditional Wizard's Hat, a powerful, handy way to generate more :mana:. Or even that treasure chest - I don't think it'd have anything in there, but I can sense it's enchanted. This must have been some Wizard's trash."
      
A Wizard's trash? Hmm... If the Wizard trashed them, isn't there a good reason why? But why would all of them be together *here*? Perhaps something happened to the Wizard...?`,
    ),
  );
  someonesTrashEventBuilder
    .message(
      t(`Your hired wizard continues: "They look terrible now, but I'm sure I can restore those items! It could be useful!"

And you also remember something you had heard before: *"Somebody's trash is somebody else's treasure".* But you don't feel very good about that...
      
Telling yourself that it's for her sake, you decide to humor her - to grab those items. But in order to do that, you need a way to go down to the canal safely - and back up, of course.`),
    )
    .option(
      t("Build a ladder (Requires Lv16 Earth, 25K Earth Essence, 3000 Mana)"),
      {
        transform: (state) => {
          state = grantResource(Resource.EarthEssence, -25000)(state);
          state = grantResource(Resource.Mana, -3000)(state);
          return state;
        },
        isEnabled: (state) => {
          return (
            getResourceAmount(state, Resource.EarthEssence) >= 25000 &&
            getResourceAmount(state, Resource.Mana) >= 3000 &&
            getElementLevel(state, SpellElement.Earth) >= 16
          );
        },
      },
      "boughtLadder",
    )
    .option(t("Maybe later"))
    .end();

  someonesTrashEventBuilder
    .message(
      t(`You carefully lay down the ladder on the side of the canal, and climb down, beginning to wade through the dirty waters.
  
You reach for the pile of stuck items, and think of which you should take first...`),
    )
    .tag("boughtLadder")
    .option(
      t("Take the Book"),
      {
        transform: (state) => someonesTrashBookStoryline.complete(state),
        isVisible: (state) => !someonesTrashBookStoryline.isCompleted(state),
      },
      "book",
    )
    .option(
      t("Take the Hat"),
      {
        transform: (state) => someonesTrashHatStoryline.complete(state),
        isVisible: (state) => !someonesTrashHatStoryline.isCompleted(state),
      },
      "hat",
    )
    .option(
      t("Take the Treasure Chest"),
      {
        transform: (state) => someonesTrashTreasureStoryline.complete(state),
        isVisible: (state) =>
          !someonesTrashTreasureStoryline.isCompleted(state),
      },
      "treasure",
    )
    .option(t("Decide later"))
    .end();

  someonesTrashEventBuilder
    .message(
      t(`You try to pull off the book from the pile. It appears to be stuck! You pull harder, but then it finally comes out - and it breaks the rest of the pile of trash, which is no longer stuck and starts floating downstream at a pretty dangerous rate! Your hired wizard yells at you and you run for the ladder. You climb with great speed and manage to make it up before the ladder, too, is swept from the current.
      
You hand over the book to your hired wizard. It is in a terrible condition. "Ugh", she says with disgust, "I should be able to restore this, but it will take me a long time. I'm sorry." You obviously understand, but that felt very dangerous. You should be more careful in the future.

**You have completed the "Somebody's Trash (Book)" Storyline! In future runs, the book will have been restored and will make you earn +25% Exp from Studying!**`),
    )
    .tag("book")
    .end();

  someonesTrashEventBuilder
    .message(
      t(`You try to pull off the hat from the pile. It appears to be stuck! You pull harder, but then it finally comes out - and it breaks the rest of the pile of trash, which is no longer stuck and starts floating downstream at a pretty dangerous rate! Your hired wizard yells at you and you run for the ladder. You climb with great speed and manage to make it up before the ladder, too, is swept from the current.
      
You hand over the hat to your hired wizard. It is in a terrible condition. "Ugh", she says with disgust, "I should be able to restore this, but it will take me a long time. I'm sorry." You obviously understand, but that felt very dangerous. You should be more careful in the future.

**You have completed the "Somebody's Trash (Hat)" Storyline! In future runs, you will start with a Wizard's Hat!**`),
    )
    .tag("hat")
    .end();

  someonesTrashEventBuilder
    .message(
      t(`You try to pull off the treasure chest from the pile. It appears to be stuck! You pull harder, but then it finally comes out - and it breaks the rest of the pile of trash, which is no longer stuck and starts floating downstream at a pretty dangerous rate! Your hired wizard yells at you and you run for the ladder. You climb with great speed and manage to make it up before the ladder, too, is swept from the current.
      
You hand over the treasure chest to your hired wizard. It is in a terrible condition. "Ugh", she says with disgust, "I should be able to restore this, but it will take me a long time. I'm sorry." You obviously understand, but that felt very dangerous. You should be more careful in the future.

**You have completed the "Somebody's Trash (Treasure)" Storyline! In future runs, the enchanted treasure chest will make you start with your Coins storage completely full!**`),
    )
    .tag("treasure")
    .end();

  const someonesTrashEvent = someonesTrashEventBuilder.build();

  registerTransformation(
    [[TransformationTags.ExpGain, TransformationTags.Study]],
    "someonesTrashBook",
    someonesTrashBookStoryline.getName(),
    TransformationType.Multiplier,
    (state) => (someonesTrashBookStoryline.isBonusActive(state) ? 1.25 : 1.0),
  );

  registerPostRetirementListener("someonesTrashHat", (state) => {
    if (someonesTrashHatStoryline.isBonusActive(state)) {
      state = addToInventory(
        { itemId: wizardsHat.getId(), params: {} },
        1,
        state,
      );
    }
    return state;
  });

  // Cannot be done via post-retirement as it has to happen after other post-retirement events
  addGameStateListener("someonesTrashTreasure", (state) => {
    if (
      someonesTrashTreasureStoryline.isBonusActive(state) &&
      !hasFlag(state, Flag.SomeonesTrashTreasureGranted)
    ) {
      return (s) => {
        s = setFlag(Flag.SomeonesTrashTreasureGranted)(s);
        s = grantResource(Resource.Coins, Infinity)(s);
        return s;
      };
    }
  });

  registerRandomEventTrigger(
    someonesTrashEvent,
    40,
    (state) =>
      !someonesTrashEvent.hasTriggered(state) &&
      [
        someonesTrashBookStoryline,
        someonesTrashHatStoryline,
        someonesTrashTreasureStoryline,
      ].some((storyline) => !storyline.isCompleted(state)) &&
      getExplorationStatus(state) != ExplorationStatus.None &&
      sewers.getDungeonFloors().includes(getCurrentDungeonFloor(state)),
  );
}

export {
  someonesTrashBookStoryline,
  someonesTrashHatStoryline,
  someonesTrashTreasureStoryline,
};
