import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Grounded extends TemporaryEffect {
  getId(): string {
    return "grounded";
  }
  getDisplayName(): string {
    return t("Grounded");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t('-40:dodge:; immune to "Airborne"');
  }
}

export const grounded = new Grounded();

export function loadGrounded() {
  TemporaryEffects.register(grounded);

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.Dodge, target]],
      "grounded" + target,
      grounded.getDisplayName(),
      TransformationType.Addition,
      (state, params) => {
        return hasTemporaryEffect(state, grounded.getId(), target) ? -40 : 0;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    grounded.getId(),
    CombatStat.Dodge,
  );

  registerTemporaryEffectListener(
    "groundedAirbornePrevention",
    (state, temporaryEffect) => {
      if (
        !hasTemporaryEffect(state, grounded.getId(), temporaryEffect.target)
      ) {
        return temporaryEffect;
      }
      if (temporaryEffect.id != "airborne") {
        return temporaryEffect;
      }
      // Prevent!
      temporaryEffect.endTime = getSecondsPlayed(state) - 1.0;
      return temporaryEffect;
    },
  );
}
