import color from "color";
import { useMemo } from "react";
import { View } from "react-native";
import { useTheme } from "react-native-paper";
import { getThemeByElementAndDarkMode } from "../../Themes";
import { SYNCHRO_INITIAL_STATE } from "../../backend/GameState";
import {
  SYNCHRO_COLS,
  SYNCHRO_ROWS,
  getSynchroPosition,
} from "../../backend/synchro/Synchro";
import Styles from "../utility/Styles";
import { useGameState } from "../utility/useGameState";

export function SynchroGridSmallDisplay(props: { gridIndex: number }) {
  const state = useGameState();
  const theme = useTheme();
  const strategy =
    state.world.synchro.grids[props.gridIndex] ?? SYNCHRO_INITIAL_STATE;

  const rows = useMemo(() => {
    const rows = [];
    for (let j = 0; j < SYNCHRO_ROWS; j++) {
      const currentRow = [];
      for (let i = 0; i < SYNCHRO_COLS; i++) {
        const element = getSynchroPosition(state, i, j, strategy);
        let backgroundColor = undefined;
        if (element != null) {
          const elementTheme = getThemeByElementAndDarkMode(
            element,
            theme.dark,
          );
          backgroundColor = color(elementTheme.colors.primaryContainer)
            .saturate(0.5)
            .lighten(theme.dark ? 0.5 : -0.5)
            .toString();
        }

        currentRow.push(
          <View
            key={i}
            style={{
              width: 8,
              height: 8,
              borderLeftWidth: 1,
              borderTopWidth: 1,
              borderColor: theme.colors.outline,
              backgroundColor,
            }}
          />,
        );
      }
      rows.push(
        <View key={j} style={Styles.flexRowAlignCenter}>
          {currentRow}
        </View>,
      );
    }
    return rows;
  }, [JSON.stringify(strategy), theme]);

  return useMemo(
    () => (
      <View
        style={{
          borderRightWidth: 1,
          borderBottomWidth: 1,
          borderColor: theme.colors.outline,
        }}
      >
        {rows}
      </View>
    ),
    [rows, theme],
  );
}
