import { valuesOf } from "../../utils/CoreUtils";
import { ActionArea, ActionSubcategory } from "../action/ActionAreas";
import {
  getVisibleActionsForAreaBySubcategory,
  registerAction,
} from "../action/Actions";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { registerTimeTickListener } from "../timetick/TimeTick";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { Enhancement } from "./Enhancement";

const Enhancements = new DataStore<Enhancement>();

export function registerEnhancement(enhancement: Enhancement) {
  Enhancements.register(enhancement);
  registerAction(enhancement);
}

export { Enhancements };

export function isEnhancementUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.EnhancementsUnlocked);
}

export function isEnhancementEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.EnhancementsUnlocked);
}

export function shouldAutoPurchaseEnhancements(state: GameState): boolean {
  return (
    hasFlag(state, Flag.AutoPurchaseUnlocked) &&
    state.options.autoPurchaseEnhancements
  );
}

export function getAllPurchasedEnhancements(
  state: GameState,
): Partial<Record<ActionSubcategory, Enhancement[]>> {
  const purchasedEnhancements = Enhancements.getAll().filter((enhancement) =>
    enhancement.isPurchased(state),
  );
  const sortedEnhancements = {};
  for (let enhancement of purchasedEnhancements) {
    const subareas = enhancement.getAreas().ENHANCEMENT ?? [];
    for (let subarea of subareas) {
      if (sortedEnhancements[subarea] == null) {
        sortedEnhancements[subarea] = [];
      }
      sortedEnhancements[subarea].push(enhancement);
    }
  }
  return sortedEnhancements;
}

export function loadEnhancementCommonLogic() {
  registerTimeTickListener(
    "enhancementAutoPurchaseMax",
    (state, timeElapsedSec, isWarp) => {
      if (!shouldAutoPurchaseEnhancements(state)) {
        return state;
      }
      const secondsPlayed = getSecondsPlayed(state);
      if (
        Math.floor(state.temp.lastAutoPurchaseEnhancementRun / 2.0) !=
        Math.floor(secondsPlayed / 2.0)
      ) {
        valuesOf(
          getVisibleActionsForAreaBySubcategory(state, ActionArea.ENHANCEMENT),
        )
          .flat()
          .forEach((action) => {
            if (action.isEnabled(state)) {
              state = action.doAction({ isAutomatic: true }, state);
            }
          });
        state.temp.lastAutoPurchaseEnhancementRun = secondsPlayed;
      }
      return state;
    },
  );
}
