import AirBlast from "./air/AirBlast";
import { blur } from "./air/Blur";
import EnchantSaleStalls from "./air/EnchantSaleStalls";
import { Haste } from "./air/Haste";
import { Levitation } from "./air/Levitation";
import richness from "./air/Richness";
import Twister from "./air/Twister";
import ChannelAir from "./channeling/ChannelAir";
import ChannelEarth from "./channeling/ChannelEarth";
import ChannelFire from "./channeling/ChannelFire";
import ChannelFour from "./channeling/ChannelFour";
import ChannelPoison from "./channeling/ChannelPoison";
import ChannelWater from "./channeling/ChannelWater";
import { earthShield } from "./earth/EarthShield";
import EnchantTransmutation from "./earth/EnchantTransmutation";
import { Guard } from "./earth/Guard";
import Knockout from "./earth/Knockout";
import Landslide from "./earth/Landslide";
import { Stoneskin } from "./earth/Stoneskin";
import StoneStrike from "./earth/StoneStrike";
import { loadElementsCommonLogic } from "./ElementsCommonLogic";
import { cloakOfFlamesSpell } from "./fire/CloakOfFlames";
import EnchantCatalysts from "./fire/EnchantCatalysts";
import Fireball from "./fire/Fireball";
import { imbueFlames } from "./fire/ImbueFlames";
import InfernalBlaze from "./fire/InfernalBlaze";
import Sear from "./fire/Sear";
import { Strength } from "./fire/Strength";
import Antidote from "./poison/Antidote";
import { enchantMonstiumCatalysts } from "./poison/EnchantMonstiumCatalysts";
import { enchantSewageSystem } from "./poison/EnchantSewageSystem";
import { frailSpell } from "./poison/Frail";
import { intoxicate } from "./poison/Intoxicate";
import { sting } from "./poison/Sting";
import { loadSpellCommonLogic } from "./SpellCommonLogic";
import { registerSpell } from "./Spells";
import DropletOfHealing from "./water/DropletOfHealing";
import EnchantManaSpouts from "./water/EnchantManaSpouts";
import enchantManaSteamers from "./water/EnchantManaSteamers";
import Frost from "./water/Frost";
import { Longevity } from "./water/Longevity";
import { soak } from "./water/Soak";
import Splash from "./water/Splash";
import StreamOfHealing from "./water/StreamOfHealing";

export function loadSpellsDemo() {
  loadElementsCommonLogic();
  loadSpellCommonLogic();

  // Fire
  registerSpell(ChannelFire);
  registerSpell(EnchantCatalysts);
  registerSpell(Fireball);
  registerSpell(Strength);
  registerSpell(Sear);
  registerSpell(enchantManaSteamers);
  registerSpell(imbueFlames);
  registerSpell(cloakOfFlamesSpell);
  registerSpell(InfernalBlaze);

  // Earth
  registerSpell(ChannelEarth);
  registerSpell(EnchantTransmutation);
  registerSpell(Stoneskin);
  registerSpell(StoneStrike);
  registerSpell(Guard);
  registerSpell(Landslide);
  registerSpell(earthShield);
  registerSpell(Knockout);

  // Water
  registerSpell(ChannelWater);
  registerSpell(EnchantManaSpouts);
  registerSpell(DropletOfHealing);
  registerSpell(Splash);
  registerSpell(Longevity);
  registerSpell(Frost);
  registerSpell(StreamOfHealing);
  registerSpell(soak);

  // Air
  registerSpell(ChannelAir);
  registerSpell(EnchantSaleStalls);
  registerSpell(AirBlast);
  registerSpell(Haste);
  registerSpell(Levitation);
  registerSpell(blur);
  registerSpell(Twister);
  registerSpell(richness);

  // Poison
  registerSpell(ChannelPoison);
  registerSpell(sting);
  registerSpell(Antidote);
  registerSpell(enchantMonstiumCatalysts);
  registerSpell(intoxicate);
  registerSpell(enchantSewageSystem);
  registerSpell(frailSpell);

  // Other
  registerSpell(ChannelFour);
}
