import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { Cherry } from "../../../items/equipment/pouch/Cherry";
import Wildgrass from "../../../items/material/Wildgrass";
import { SpellElement } from "../../../spells/Elements";
import { standardEnemyAttackEffect } from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class FlyingRock extends Enemy {
  getId(): string {
    return "flyingRock";
  }

  getName(): string {
    return t("Flying Rock");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 45,
      attack: 16,
      defense: 50,
      attackDelay: 3.6,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./flyingRock.png");
  }

  getLevel(): number {
    return 2;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 16;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 3;
  }

  getNextAction(state: GameState): BattlerAction {
    if (Math.random() < 0.2) {
      return {
        name: t("Stone Strike"),
        delay: this.getAttackDelay(state) * 1.3,
        transform: (state) =>
          standardEnemyAttackEffect(state, this.getAttack(state) * 1.5, {
            tags: [SpellElement.Earth],
            cause: t("Stone Strike"),
          }),
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 1,
        chance: 0.4,
      },
      {
        itemId: Cherry.getId(),
        amount: 1,
        chance: 0.01,
      },
    ];
  }
}

export default new FlyingRock();
