import { Entypo } from "@expo/vector-icons";
import React, {
  useCallback,
  useContext,
  useMemo,
  useRef,
  useState,
} from "react";
import { StyleSheet, View } from "react-native";
import { TouchableOpacity } from "react-native-gesture-handler";
import { Portal, Surface, useTheme } from "react-native-paper";
import { buildFamiliarCrate } from "../backend/buildings/FamiliarCrate";
import { DungeonFloors } from "../backend/exploration/dungeons/Dungeons";
import {
  FamiliarStatusType,
  Familiars,
  deleteFamiliar,
  getFamiliarData,
  setAllFamiliarsToAutoExplore,
  setAllFamiliarsToExplore,
  setCurrentCompanion,
  setFamiliarToAutoExplore,
  setFamiliarToExplore,
  translateFamiliarCompanionStatusType,
} from "../backend/familiars/Familiar";
import { keysOf } from "../utils/CoreUtils";
import { formatNumber } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import { EnemySpriteTile } from "./exploration/EnemySpriteTile";
import { FamiliarMessageLog } from "./familiars/FamiliarMessageLog";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import { CombatStatsViewer } from "./utility/CombatStatsViewer";
import { DungeonFloorSelect } from "./utility/DungeonFloorSelect";
import { FullscreenModal } from "./utility/FullscreenModal";
import { GameStateFreezer } from "./utility/GameStateFreezer";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { UIContext, useUIStateFromContext } from "./utility/UIContext";
import { useGameState } from "./utility/useGameState";

function FamiliarRow(props: { familiarId: string }) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const theme = useTheme();

  const [collapsed, setCollapsed] = useState(true);

  const familiarId = props.familiarId;
  const familiarData = getFamiliarData(state, familiarId);
  const familiar = Familiars.getById(familiarData.familiarClassId);

  const familiarLevel = familiar.getLevel(state, familiarId);
  const familiarMaxLevel = familiar.getMaxLevel(state, familiarId);

  let statusString = t("Idle");
  switch (familiarData.status.status) {
    case FamiliarStatusType.Companion:
      statusString = t("Fighting ({{companionStatus}})", {
        companionStatus: translateFamiliarCompanionStatusType(
          familiarData.status.companionStatus,
        ),
      });
      break;
    case FamiliarStatusType.Exploring:
      statusString = t(
        "{{auto}}Exploring ({{dungeonFloorName}} - {{explorationProgressPct}}%)",
        {
          auto: familiarData.status.autoExploring ? t("Auto-") : "",
          dungeonFloorName: DungeonFloors.getById(
            familiarData.status.exploringDungeonFloorId,
          ).getFloorName(),
          explorationProgressPct: formatNumber(
            familiarData.status.actionProgress * 100,
          ),
        },
      );
      break;
  }

  const uiContext = useContext(UIContext);
  const [_isModalSelectorOpen, setIsModalSelectorOpen] = useUIStateFromContext(
    uiContext,
    "picker_open",
    false,
  );
  const [showingDungeonSelector, setShowingDungeonSelector] = useState(false);

  const showModal = useCallback(() => {
    setShowingDungeonSelector(true);
    setIsModalSelectorOpen(true);
  }, [setShowingDungeonSelector, setIsModalSelectorOpen]);
  const hideModal = useCallback(() => {
    setShowingDungeonSelector(false);
    setIsModalSelectorOpen(false);
  }, [setShowingDungeonSelector, setIsModalSelectorOpen]);

  const [confirmRelease, setConfirmRelease] = useState(false);
  const releaseFamiliar = useCallback(() => {
    if (confirmRelease) {
      stateRef.current.apply((state) => {
        state = deleteFamiliar(state, familiarId);
        buildFamiliarCrate._demolishUnsafe(state, 1);
        return state;
      });
    } else {
      setConfirmRelease(true);
    }
  }, [confirmRelease]);

  const autoExploreCallback = useCallback(() => {
    stateRef.current.apply((state) =>
      setFamiliarToAutoExplore(state, familiarId),
    );
  }, [familiarId]);
  const companionCallback = useCallback(() => {
    stateRef.current.apply((state) => setCurrentCompanion(state, familiarId));
  }, [familiarId]);
  const explorationCallback = useCallback(
    (dungeonFloorId: string) => {
      stateRef.current.apply((state) =>
        setFamiliarToExplore(state, familiarId, dungeonFloorId),
      );
      hideModal();
    },
    [familiarId, hideModal],
  );

  return useMemo(
    () => (
      <Surface mode="flat" key={props.familiarId} style={styles.rowContainer}>
        <TouchableOpacity
          onPress={() => setCollapsed(!collapsed)}
          style={styles.expandTouchable}
        >
          <View style={Styles.flexRowSpaceBetween}>
            <View>
              <Text h4 style={Styles.bold}>
                {t("{{familiarName}} - Lv{{familiarLevel}}", {
                  familiarName: familiar.getName(),
                  familiarLevel: familiarLevel,
                })}
              </Text>
              <Text>{statusString}</Text>
            </View>
            <Entypo
              name={collapsed ? "chevron-right" : "chevron-down"}
              size={24}
              color={theme.colors.onBackground}
            />
          </View>
        </TouchableOpacity>
        <GameStateFreezer isVisible={!collapsed}>
          <View style={collapsed ? { display: "none" } : null}>
            <View style={Styles.flexRowSpaceBetween}>
              <View style={[Styles.flex, Styles.flexCenter]}>
                <EnemySpriteTile sprite={familiar.getPicture(state)} />
              </View>
              <View style={Styles.flex}>
                <CategoryContainer title={t("General")}>
                  <View style={Styles.mt8}>
                    <View style={Styles.flexRowSpaceBetween}>
                      <Text>{t("Level:")}</Text>
                      {familiarLevel < familiarMaxLevel ? (
                        <Text>
                          {t("{{level}}", {
                            level: formatNumber(familiarLevel),
                          })}
                        </Text>
                      ) : (
                        <Text>
                          {t("{{level}} (Max)", {
                            level: formatNumber(familiarLevel),
                          })}
                        </Text>
                      )}
                    </View>
                    <View style={Styles.flexRowSpaceBetween}>
                      <Text>{t("Exp:")}</Text>
                      <Text>
                        {t("{{currentExp}}/{{toNextLevel}}", {
                          currentExp: formatNumber(familiarData.exp),
                          toNextLevel: formatNumber(
                            familiar.getExpForNextLevel(state, familiarId),
                          ),
                        })}
                      </Text>
                    </View>
                    <View style={Styles.flexRowSpaceBetween}>
                      <Text>{t("Status:")}</Text>
                      <Text
                        style={[Styles.ml8, { textAlign: "right", flex: 1 }]}
                      >
                        {statusString}
                      </Text>
                    </View>
                  </View>
                </CategoryContainer>
                <CategoryContainer title={t("Stats")}>
                  <View style={Styles.mt8}>
                    <CombatStatsViewer
                      combatStats={{
                        ...familiar.getStats(state, familiarId),
                        currentHP: familiarData.status.currentHp,
                      }}
                      showCurrentHP={true}
                    />
                  </View>
                </CategoryContainer>
              </View>
            </View>
            <CategoryContainer title={t("Actions")}>
              <View style={Styles.mt8}>
                <View style={styles.optionRow}>
                  <Button
                    onPress={companionCallback}
                    disabled={
                      familiarData.status.status == FamiliarStatusType.Companion
                    }
                    compact={true}
                  >
                    {t("Summon")}
                  </Button>
                  <Button onPress={showModal} compact={true}>
                    {t("Explore")}
                  </Button>
                  <Button
                    onPress={autoExploreCallback}
                    disabled={
                      familiarData.status.status ==
                        FamiliarStatusType.Exploring &&
                      familiarData.status.autoExploring
                    }
                    compact={true}
                  >
                    {t("Auto-Explore")}
                  </Button>
                  <Portal>
                    <FullscreenModal
                      visible={showingDungeonSelector}
                      onDismiss={hideModal}
                    >
                      <DungeonFloorSelect
                        onSelect={explorationCallback}
                        familiarId={familiarId}
                      />
                    </FullscreenModal>
                  </Portal>
                </View>
              </View>
              <View style={[Styles.mt8, Styles.ph8]}>
                <CategoryContainer
                  title={t("Release")}
                  initiallyExpanded={false}
                >
                  <View style={Styles.mt8}>
                    <Markdown>
                      {t(
                        "If you want to recover the land used by a Familiar Crate, you can release a familiar. However, you will **lose the familiar** permanently - if you build a Familiar Crate again, you will need to raise the familiar again as if it was new.",
                      )}
                    </Markdown>
                    <View style={[Styles.mt8, Styles.flexCenter]}>
                      <Button onPress={releaseFamiliar}>
                        {confirmRelease
                          ? t("Press again to confirm")
                          : t("Release Familiar")}
                      </Button>
                    </View>
                  </View>
                </CategoryContainer>
              </View>
            </CategoryContainer>
            <CategoryContainer title={t("Activity")} initiallyExpanded={false}>
              <View style={[Styles.mt8, styles.messageLogContainer]}>
                <FamiliarMessageLog familiarId={familiarId} />
              </View>
            </CategoryContainer>
          </View>
        </GameStateFreezer>
      </Surface>
    ),
    [
      collapsed,
      setCollapsed,
      familiar.getName(),
      familiarLevel,
      familiarMaxLevel,
      statusString,
      !collapsed && showingDungeonSelector,
      !collapsed && hideModal,
      !collapsed && explorationCallback,
      !collapsed && autoExploreCallback,
      !collapsed && JSON.stringify(familiarData),
      theme,
      confirmRelease,
      releaseFamiliar,
    ],
  );
}

export function FamiliarsComponent() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const familiarIds = keysOf(state.run.familiars);

  const uiContext = useContext(UIContext);
  const [_isModalSelectorOpen, setIsModalSelectorOpen] = useUIStateFromContext(
    uiContext,
    "picker_open",
    false,
  );
  const [showingDungeonSelector, setShowingDungeonSelector] = useState(false);

  const showModal = useCallback(() => {
    setShowingDungeonSelector(true);
    setIsModalSelectorOpen(true);
  }, [setShowingDungeonSelector, setIsModalSelectorOpen]);
  const hideModal = useCallback(() => {
    setShowingDungeonSelector(false);
    setIsModalSelectorOpen(false);
  }, [setShowingDungeonSelector, setIsModalSelectorOpen]);

  const autoExploreCallback = useCallback(() => {
    stateRef.current.apply((state) => setAllFamiliarsToAutoExplore(state));
  }, []);
  const explorationCallback = useCallback(
    (dungeonFloorId: string) => {
      stateRef.current.apply((state) =>
        setAllFamiliarsToExplore(state, dungeonFloorId),
      );
      hideModal();
    },
    [hideModal],
  );

  return useMemo(() => {
    return (
      <View style={styles.topContainer}>
        {familiarIds.length > 0 ? (
          <View>
            {familiarIds.map((familiarId) => (
              <View key={familiarId} style={Styles.pv4}>
                <FamiliarRow familiarId={familiarId} key={familiarId} />
              </View>
            ))}

            <View style={Styles.mt16}>
              <CategoryContainer title={t("Familiar Actions")}>
                <View style={Styles.flexCenter}>
                  <View
                    style={[
                      Styles.mt8,
                      Styles.flexRowAlignCenter,
                      { gap: 8, justifyContent: "space-around" },
                    ]}
                  >
                    <Button onPress={showModal} compact={true}>
                      {t("All Explore")}
                    </Button>
                    <Button onPress={autoExploreCallback} compact={true}>
                      {t("All Auto-Explore")}
                    </Button>
                  </View>
                </View>
                <Portal>
                  <FullscreenModal
                    visible={showingDungeonSelector}
                    onDismiss={hideModal}
                  >
                    <DungeonFloorSelect
                      onSelect={explorationCallback}
                      familiarId={undefined}
                    />
                  </FullscreenModal>
                </Portal>
              </CategoryContainer>
            </View>
          </View>
        ) : (
          <Markdown>
            {t(
              "In this screen, you can check the status of your familiars and control them. However, you have no familiars right now. Build a Familiar Crate in your Home to get one!",
            )}
          </Markdown>
        )}
      </View>
    );
  }, [
    familiarIds.join(),
    showingDungeonSelector,
    hideModal,
    explorationCallback,
    autoExploreCallback,
    showModal,
  ]);
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
  },
  contentContainer: {
    paddingVertical: 8,
    paddingHorizontal: 4,
    alignItems: "stretch",
    justifyContent: "flex-start",
  },
  hintProgress: {
    marginTop: 4,
  },
  loadoutsContainer: {
    borderTopWidth: 1,
    paddingVertical: 8,
    paddingHorizontal: 4,
  },
  categoryTitle: {
    marginLeft: 8,
  },
  categoryContainer: {
    paddingTop: 4,
    marginBottom: 8,
  },
  campusContainer: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
  },
  campusHintWarning: {
    marginTop: 16,
  },
  optionRow: {
    marginTop: 8,
    flexDirection: "row",
    flexWrap: "wrap",
    gap: 8,
    justifyContent: "space-around",
  },
  overlay: {
    alignSelf: "center",
    marginHorizontal: 16,
    maxWidth: 640,
  },
  messageLogContainer: {
    padding: 8,
    borderColor: "#444",
    borderWidth: 1,
    maxHeight: 320,
  },
  rowContainer: {
    padding: 8,
    borderRadius: 8,
  },
  topContainer: {
    gap: 8,
  },
  expandTouchable: {
    // @ts-ignore This is a valid property
    cursor: "pointer",
  },
});
