import { battlemage } from "./Battlemage";
import { builder } from "./Builder";
import { CombatClasses } from "./CombatClasses";
import { loadCombatClassesUnlocker } from "./CombatClassesUnlocker";
import { spellslinger } from "./Spellslinger";

export function loadCombatClassesDemo() {
  CombatClasses.register(battlemage);
  CombatClasses.register(spellslinger);
  CombatClasses.register(builder);

  loadCombatClassesUnlocker();
}
