import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { Enhancement } from "../Enhancement";

class TreasureChests extends Enhancement {
  getId(): string {
    return "treasureChests";
  }
  getBaseName(): string {
    return t("Safes");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_STORAGE;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 400,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 1,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Discover a new type of furniture, that allows safeguarding of a large amount of :coins:.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t('Unlock furniture "Safe"');
  }
}

const treasureChests = new TreasureChests();

export { treasureChests };
