import { ActionArea, ActionSubcategory } from "../action/ActionAreas";
import { ExplorationStatus } from "../exploration/ExplorationStatus";
import { getExplorationStatus } from "../exploration/ExplorationUtils";
import { Flag } from "../Flag";
import { hasEverHadFlag } from "../Flags";
import { GameState } from "../GameState";
import { Spell } from "./Spell";

export abstract class CombatSpellBase extends Spell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return {
      SPELL_MENU: [this.getElement()],
      EXPLORATION: [this.getElement()],
    };
  }

  isVisible(state: GameState): boolean {
    return (
      hasEverHadFlag(state, Flag.ExplorationUnlocked) && super.isVisible(state)
    );
  }

  canUseOutsideOfCombat(): boolean {
    return false;
  }

  isEnabled(state: GameState, skipAffordabilityChecks?: boolean): boolean {
    return (
      (this.canUseOutsideOfCombat() ||
        getExplorationStatus(state) == ExplorationStatus.Combat) &&
      super.isEnabled(state, skipAffordabilityChecks)
    );
  }
}
