import React, { useCallback, useMemo, useRef, useState } from "react";
import { StyleSheet, View } from "react-native";
import { Portal, Switch } from "react-native-paper";
import {
  isItemBeingAutosold,
  toggleItemAutosold,
} from "../../backend/autocast/Market";
import { isMarketUnlocked } from "../../backend/buildings/SaleStall";
import {
  ItemAction,
  ItemOccurrence,
  ItemStack,
} from "../../backend/items/Item";
import { Items } from "../../backend/items/Items";
import { FamiliarItem } from "../../backend/items/consumable/FamiliarItem";
import { t } from "../../utils/I18n";
import { FullscreenModal } from "../utility/FullscreenModal";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";
import { ItemDetailsAction } from "./ItemDetailsAction";
import { ItemFamiliarModalContents } from "./ItemFamiliarModalContents";
import { ItemInfo } from "./ItemInfo";

export interface ItemDetailsProps {
  itemStack: ItemStack;
  selectItem: (item: ItemOccurrence) => void;
  disableSelling?: boolean;
  showQuickbar?: boolean;
}

export function ItemDetails(props: ItemDetailsProps) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const { itemId, params } = props.itemStack.itemOccurrence;
  const item = Items.getById(itemId);
  const name = item.getName(params);
  const description = item.getDescription(state, params);
  const effect = item.getEffect(state, params);

  const isFamiliarItem = item instanceof FamiliarItem;
  const [familiarItemModalVisible, setFamiliarItemModalVisible] =
    useState(false);
  const dismissFamiliarItemModal = useCallback(
    () => setFamiliarItemModalVisible(false),
    [],
  );
  const familiarPrimaryAction = useMemo(
    () => ({
      id: "familiarPrimary",
      description: t("Use"),
      isEnabled: true,
      transform: (state) => {
        setFamiliarItemModalVisible(true);
        return state;
      },
    }),
    [setFamiliarItemModalVisible],
  );

  const disableSelling =
    props.disableSelling || item.getBaseSalePrice(state, params) == 0;

  const primaryAction: ItemAction = isFamiliarItem
    ? familiarPrimaryAction
    : item.getPrimaryAction(state, params);
  const saleActions = disableSelling
    ? undefined
    : item.getSaleActions(state, params);
  const quickbarAction = props.showQuickbar
    ? item.getQuickbarAction(state, params)
    : undefined;
  const combineActions = props.disableSelling
    ? undefined
    : item.getCombineActions(state, params);
  const extraActions = item.getExtraActions(state, params);

  const onCombineAllExtraPress = useCallback(() => {
    const newQuality =
      (props.itemStack.itemOccurrence.params.itemQuality ?? 0) + 1;
    const newOccurrence = {
      itemId: props.itemStack.itemOccurrence.itemId,
      params: {
        ...props.itemStack.itemOccurrence.params,
        itemQuality: newQuality,
      },
    };
    props.selectItem(newOccurrence);
  }, [props.itemStack.itemOccurrence, props.selectItem]);

  const actions = [
    { action: primaryAction },
    ...(saleActions?.map((saleAction) => ({ action: saleAction })) ?? []),
    { action: quickbarAction },
    ...(combineActions?.map((combineAction, idx) => ({
      action: combineAction,
      extraPress: idx == combineActions.length - 1 && onCombineAllExtraPress,
    })) ?? []),
    ...(extraActions?.map((action) => ({ action: action })) ?? []),
  ].filter((action) => action.action != undefined) as {
    action: ItemAction;
    requireConfirm?: boolean;
    extraPress?: () => void;
  }[];

  const itemInfo = useMemo(() => {
    return <ItemInfo itemStack={props.itemStack} showTile={true} />;
  }, [JSON.stringify(props.itemStack)]);

  const onAutosoldPress = useCallback(() => {
    stateRef.current.apply((s) =>
      toggleItemAutosold(s, props.itemStack.itemOccurrence),
    );
  }, [props.itemStack.itemOccurrence]);

  return (
    <View style={styles.itemDetailsContainer}>
      {itemInfo}
      {isMarketUnlocked(state) && !disableSelling && (
        <View style={[Styles.mt8, Styles.flexRowAlignCenter, Styles.flex]}>
          <Text>{t("Sell automatically through Market Stalls")}</Text>
          <Switch
            style={Styles.ml8}
            value={isItemBeingAutosold(state, props.itemStack.itemOccurrence)}
            onValueChange={onAutosoldPress}
          />
        </View>
      )}
      <View style={styles.actionRow}>
        {actions.map((action) => (
          <ItemDetailsAction
            key={action.action.id}
            action={action.action}
            extraOnPress={action.extraPress}
          />
        ))}
      </View>
      {isFamiliarItem && (
        <Portal>
          <FullscreenModal
            visible={familiarItemModalVisible}
            onDismiss={dismissFamiliarItemModal}
          >
            <ItemFamiliarModalContents
              item={item as FamiliarItem}
              onClose={dismissFamiliarItemModal}
              params={params}
            />
          </FullscreenModal>
        </Portal>
      )}
    </View>
  );
}

const styles = StyleSheet.create({
  itemDetailsContainer: {
    marginBottom: 16,
  },
  actionRow: {
    flexDirection: "row",
    flexWrap: "wrap",
    marginTop: 8,
    rowGap: 8,
  },
  actionButtonContainer: {
    marginRight: 8,
  },
});
