import { t } from "../../../../utils/I18n";
import { createChestEvent } from "../../../events/autorandom/Chest";
import { ExplorationTriggerGroup } from "../../../events/autorandom/ExplorationTrigger";
import { Flag } from "../../../Flag";
import { hasFlag } from "../../../Flags";
import { GameState } from "../../../GameState";
import { copperGauntlet } from "../../../items/equipment/hand/air/CopperGauntlet";
import { copperRapier } from "../../../items/equipment/hand/earth/CopperRapier";
import { copperTomahawk } from "../../../items/equipment/hand/fire/CopperTomahawk";
import { antidotePotion } from "../../../items/equipment/pouch/AntidotePotion";
import { copperOre } from "../../../items/transmute/materials/CopperOre";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { Dungeon } from "../Dungeon";
import { DungeonFloor } from "../DungeonFloor";
import SewerEntrance from "./SewerEntrance";
import SewerHazardousSector from "./SewerHazardousSector";
import SewerHideout from "./SewerHideout";

class Sewers extends Dungeon {
  getId(): string {
    return "sewers";
  }

  getName(): string {
    return t("Sewers");
  }

  getDungeonFloors(): DungeonFloor[] {
    return [SewerEntrance, SewerHazardousSector, SewerHideout];
  }

  isUnlocked(state: GameState): boolean {
    return hasFlag(state, Flag.SewersUnlocked);
  }
}

export const sewers = new Sewers();

class SewersExplorationGroup extends ExplorationTriggerGroup {
  getHintMessageWhenPossible(): string {
    return t("Explore the Sewers");
  }
  getHintMessageWhenNotPossible(): string {
    return t(
      "This Storyline can be found by exploring the Sewers, but you cannot get it this run",
    );
  }
  maxTriggersPerRun(): number {
    return 1;
  }
  getId(): string {
    return "sewers";
  }
  getExplorationSuccessesBase(): number {
    return 150;
  }
  getDungeonFloors(): DungeonFloor[] {
    return sewers.getDungeonFloors();
  }
}

export const sewersExplorationGroup = new SewersExplorationGroup();

export function loadSewerChests() {
  createChestEvent(
    "sewerChestCommon1",
    [SewerEntrance.getId(), SewerHazardousSector.getId(), SewerHideout.getId()],
    70,
    [
      {
        itemOccurrence: { itemId: copperOre.getId(), params: {} },
        amount: 8,
      },
    ],
  );
  createChestEvent(
    "sewerChestCommon2",
    [SewerEntrance.getId(), SewerHazardousSector.getId(), SewerHideout.getId()],
    50,
    [
      {
        itemOccurrence: { itemId: cotton.getId(), params: {} },
        amount: 3,
      },
    ],
  );

  createChestEvent(
    "sewerChestRare0",
    [SewerEntrance.getId(), SewerHazardousSector.getId(), SewerHideout.getId()],
    4,
    [
      {
        itemOccurrence: { itemId: antidotePotion.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "sewerChestRare1",
    [SewerEntrance.getId(), SewerHazardousSector.getId(), SewerHideout.getId()],
    4,
    [
      {
        itemOccurrence: { itemId: copperTomahawk.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "sewerChestRare2",
    [SewerEntrance.getId(), SewerHazardousSector.getId(), SewerHideout.getId()],
    4,
    [
      {
        itemOccurrence: { itemId: copperGauntlet.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "sewerChestRare3",
    [SewerEntrance.getId(), SewerHazardousSector.getId(), SewerHideout.getId()],
    4,
    [
      {
        itemOccurrence: { itemId: copperRapier.getId(), params: {} },
        amount: 1,
      },
    ],
  );
}
