import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { GameState } from "../GameState";
import { Identifiable } from "../generic/Identifiable";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { TransformationType } from "../transformation/Transformation";
import {
  getTemporaryEffectEndTime,
  TemporaryEffectData,
} from "./TemporaryEffects";

export abstract class TemporaryEffect implements Identifiable {
  abstract getId(): string;

  abstract getDisplayName(): string;

  abstract getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string;

  getParameterlessDisplayDescription(state: GameState): string {
    return this.getDisplayDescription(state, {
      id: "null",
      fromPouch: false,
      fromPermacast: false,
      fromAutocast: false,
      endTime: getSecondsPlayed(state),
      setTime: getSecondsPlayed(state),
      params: {},
      registeredClear: false,
      target: AttackTarget.Player,
    });
  }

  isCommonBuffEnabled(): { stat: CombatStat; type: TransformationType }[] {
    return [];
  }

  getIcon() {
    if (this.isBeneficial()) {
      return require("./buff.png");
    } else {
      return require("./debuff.png");
    }
  }

  isBeneficial(): boolean {
    return true;
  }

  isCombatOnly(): boolean {
    return false;
  }

  isDispellable(): boolean {
    return true;
  }

  isHidden(): boolean {
    return false;
  }

  // When a temporary effect is applied with the same ID, the data must be merged
  mergeData(
    state: GameState,
    oldData: TemporaryEffectData,
    newData: TemporaryEffectData,
    mode: "add" | "replace",
  ): TemporaryEffectData {
    if (mode == "add") {
      const currentEndTime = getTemporaryEffectEndTime(
        state,
        this.getId(),
        oldData.target,
      );
      const extraDuration = Math.max(
        0,
        currentEndTime - getSecondsPlayed(state),
      );
      newData.endTime += extraDuration;
    }
    return newData;
  }
}
