import { registerSpell } from "../spells/Spells";
import { Buildings } from "./Buildings";
import { loadEssenceCatalysts } from "./catalysts/EssenceCatalyst";
import { loadMonstiumCatalysts } from "./catalysts/MonstiumCatalyst";
import { BuildManaSpout, ManaSpout } from "./mana/ManaSpout";
import { buildManaSteamer, manaSteamer } from "./mana/ManaSteamer";
import { loadSewageSystem } from "./production/SewageSystem";
import { buildResearcherDesk, researcherDesk } from "./ResearcherDesk";
import { BuildSaleStall, SaleStall } from "./SaleStall";
import { loadEssenceStorages } from "./storages/EssenceStorage";
import { BuildManaShard, ManaShard } from "./storages/ManaShard";
import { loadMonstruarium } from "./storages/Monstruarium";
import { loadTreasureChest } from "./storages/TreasureChest";
import { BuildWizardChamber, WizardChamber } from "./WizardChamber";

export function loadBuildingsDemo() {
  Buildings.register(ManaSpout);
  registerSpell(BuildManaSpout);
  Buildings.register(ManaShard);
  registerSpell(BuildManaShard);

  loadEssenceStorages();
  loadEssenceCatalysts();

  Buildings.register(WizardChamber);
  registerSpell(BuildWizardChamber);
  Buildings.register(SaleStall);
  registerSpell(BuildSaleStall);
  Buildings.register(researcherDesk);
  registerSpell(buildResearcherDesk);

  loadTreasureChest();
  Buildings.register(manaSteamer);
  registerSpell(buildManaSteamer);

  loadMonstruarium();
  loadSewageSystem();
  loadMonstiumCatalysts();
}
