import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { CombatActionResultListeners } from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  clearTemporaryEffect,
  getTemporaryEffectData,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class Guard extends TemporaryEffect {
  getId(): string {
    return "guard";
  }
  getDisplayName(): string {
    return t("Guard");
  }
  isBeneficial(): boolean {
    return true;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("-{{amount}}% damage received from next attack", {
      amount: formatNumber(100 * (1.0 - temporaryEffectData.params.multiplier)),
    });
  }
}

const guard = new Guard();

export { guard as GuardTemporaryEffect };

export function loadGuard() {
  TemporaryEffects.register(guard);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "guard",
    guard.getDisplayName(),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(state, guard.getId(), params.target)
        ? getTemporaryEffectData(state, guard.getId(), params.target).params
            .multiplier
        : 1.0;
    },
  );

  CombatActionResultListeners.register((state, result) => {
    if (
      result.isHit &&
      result.damage >= 0 &&
      hasTemporaryEffect(state, guard.getId(), result.target)
    ) {
      state = clearTemporaryEffect(guard.getId(), result.target)(state);
    }
    return state;
  });
}
