import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { DataStore } from "../generic/DataStore";
import { Identifiable } from "../generic/Identifiable";
import { getGameEventById } from "./Events";
import { GameEvent } from "./GameEvent";

export type DungeonEvent = {
  event: GameEvent;
  getWeight: (state: GameState) => number;
};

export class DungeonEventGroup implements Identifiable {
  id: string;
  events: DungeonEvent[] = [];

  constructor(id: string) {
    this.id = id;
  }

  getId(): string {
    return this.id;
  }

  addEvent(event: DungeonEvent): DungeonEventGroup {
    this.events.push(event);
    return this;
  }

  getSelectedEvent(state: GameState): GameEvent | undefined {
    return getGameEventById(
      state.run.variables?.["dungeonEventGroup_" + this.id] as
        | string
        | undefined,
    );
  }

  hasFinishedSelecting(state: GameState): boolean {
    return state.run.variables["dungeonEventGroup_" + this.id] != null;
  }

  hasFinishedTriggeringThisRun(state: GameState): boolean {
    const event = this.getSelectedEvent(state);
    const triggered = event == null || event.hasTriggered(state);
    return triggered;
  }

  selectEvent(state: GameState): GameState {
    if (this.hasFinishedSelecting(state)) {
      return state;
    }

    const totalWeight = this.events.reduce(
      (sum, current) => sum + current.getWeight(state),
      0,
    );
    if (totalWeight <= 0) {
      state.run.variables["dungeonEventGroup_" + this.id] =
        "dungeonEventGroup_allCompleted";
      return state;
    }

    let randomValue = Math.random() * totalWeight;
    for (let event of this.events) {
      randomValue -= event.getWeight(state);
      if (randomValue < 0) {
        state.run.variables["dungeonEventGroup_" + this.id] =
          event.event.getId();
        return state;
      }
    }

    return state;
  }
}

const DungeonEventGroups = new DataStore<DungeonEventGroup>();

export function getOrCreateDungeonEventGroup(id: string): DungeonEventGroup {
  let dungeonEventGroup = DungeonEventGroups.getByIdNullable(id);
  if (dungeonEventGroup == null) {
    dungeonEventGroup = new DungeonEventGroup(id);
    DungeonEventGroups.register(dungeonEventGroup);
  }
  return dungeonEventGroup;
}

export function loadDungeonEventGroups() {
  addGameStateListener("dungeonEventGroupTriggerer", (state) => {
    DungeonEventGroups.getAll()
      .filter((group) => !group.hasFinishedSelecting(state))
      .forEach((group) => group.selectEvent(state));
  });
}
