import { clone } from "../utils/CoreUtils";
import { clearAllCaches } from "./Caching";
import { hasWorldFlag } from "./Flags";
import { GameState, RUN_INITIAL_STATE } from "./GameState";

export type RetirementListenerTransform = (
  state: GameState,
  isMock: boolean,
) => GameState;
const retirementListeners: Record<string, RetirementListenerTransform> = {};
const postRetirementListeners: Record<string, RetirementListenerTransform> = {};

export function registerRetirementListener(
  id: string,
  listener: RetirementListenerTransform,
) {
  retirementListeners[id] = listener;
}

export function registerPostRetirementListener(
  id: string,
  listener: RetirementListenerTransform,
) {
  postRetirementListeners[id] = listener;
}

export function isRetirementUnlocked(state: GameState): boolean {
  return hasWorldFlag(state, "storyline_posterity_completed");
}

export function retire(state: GameState, isMock: boolean) {
  // Invalidate caches to prevent problems
  state.global.tickCount++;
  state = clearAllCaches(state);

  Object.keys(retirementListeners).forEach((listenerId) => {
    state = retirementListeners[listenerId](state, isMock);
  });

  state.world.totalTimesResetted += 1;

  let newState = clone(RUN_INITIAL_STATE);
  newState.lastProcessedTime = Date.now();
  newState.startTimestamp = Date.now();
  state.run = newState;

  // Invalidate caches again to prevent problems
  state.global.tickCount++;
  state = clearAllCaches(state);

  Object.keys(postRetirementListeners).forEach((listenerId) => {
    state = postRetirementListeners[listenerId](state, isMock);
  });

  // Invalidate caches one more time to prevent problems
  state.global.tickCount++;
  state = clearAllCaches(state);

  return state;
}

function flagsMover(state: GameState): GameState {
  for (let flag in state?.run?.newFlagsOnRetirement || {}) {
    state.world.flags[flag] = true;
  }
  return state;
}

registerRetirementListener("new_flags", flagsMover);
