import AsyncStorage from "@react-native-async-storage/async-storage";
import { Buffer } from "buffer";
import deepmerge from "deepmerge";
import zlib from "react-zlib-js";
import { isArray, promisify } from "util";
import { clone } from "../utils/CoreUtils";
import {
  AUTOCAST_INITIAL_STATE,
  createTempState,
  GameState,
  INITIAL_STATE,
} from "./GameState";
import { EquipmentSlot } from "./items/Equipment";
import { getTotalSecondsPlayed } from "./timetick/TotalTimePlayed";

const gzipCompress = promisify(zlib.gzip);
const gzipDecompress = promisify(zlib.gunzip);

export async function loadData(): Promise<GameState> {
  const jsonValue = await AsyncStorage.getItem("MR2.GameState");
  if (jsonValue !== null) {
    const state = JSON.parse(jsonValue);

    // Fix all the save compatibility changes
    return makeCompatible(state);
  }
  return clone(INITIAL_STATE);
}

export async function saveData(state: GameState): Promise<void> {
  const lastSaveDataTime = Math.max(
    Number.parseFloat(
      (await AsyncStorage.getItem("MR2.LastSaveSecondsPlayed")) || "0",
    ),
    0,
  );
  if (Math.max(getTotalSecondsPlayed(state), 0) >= lastSaveDataTime) {
    await saveDataUnsafe(state);
  } else {
    throw new Error("Not saving!!");
  }
}

export async function saveDataUnsafe(state: GameState): Promise<void> {
  // Do not save temp
  const oldTemp = state.temp;
  // @ts-ignore We are doing this for saving only
  state.temp = {};
  const jsonValue = JSON.stringify(state);
  state.temp = oldTemp;
  await AsyncStorage.setItem(
    "MR2.LastSaveSecondsPlayed",
    Math.max(getTotalSecondsPlayed(state), 0).toString(),
  );
  await AsyncStorage.setItem("MR2.GameState", jsonValue);
}

export function makeCompatible(obj: GameState): GameState {
  if (obj.global.autocast == null) {
    obj.global.autocast = AUTOCAST_INITIAL_STATE;
    obj.global.autocast.currentStrategy =
      // @ts-expect-error These values were moved
      clone(obj.run.autocast.strategies[obj.run.autocast.currentStrategyIndex]);
    // @ts-expect-error These values were moved
    delete obj.run.autocast.strategies;
    // @ts-expect-error These values were moved
    delete obj.run.autocast.currentStrategyIndex;
  }

  for (let rule of obj.global.autocast.currentStrategy.rules) {
    if (rule.id == null) {
      rule.id = Math.random().toString();
    }
  }

  for (let slot in EquipmentSlot) {
    if (!isArray(obj.run.equipment[slot])) {
      obj.run.equipment[slot] =
        obj.run.equipment[slot] != null ? [obj.run.equipment[slot]] : [];
    }
    obj.run.equipmentLoadouts.forEach((loadout) => {
      if (loadout != null && !isArray(loadout.equipment[slot])) {
        loadout.equipment[slot] =
          loadout.equipment[slot] != null ? [loadout.equipment[slot]] : [];
      }
    });
  }

  // Dp not load temp
  obj.temp = createTempState();
  return deepmerge(clone(INITIAL_STATE), obj);
}

export async function loadBase64Data(
  saveData: string,
): Promise<GameState | null> {
  const decodedBuffer = Buffer.from(
    Buffer.from(saveData, "base64").toString(),
    "base64",
  );
  const decodedJSON = await gzipDecompress(decodedBuffer);
  const decodedData = JSON.parse(decodedJSON.toString());
  if (decodedData !== null) {
    return makeCompatible(decodedData);
  }
  return null;
}

export async function serializeSaveData(state: GameState): Promise<string> {
  // Do not save temp
  const oldTemp = state.temp;
  // @ts-ignore We are doing this for serialization only
  state.temp = {};
  const jsonValue = JSON.stringify(state);
  state.temp = oldTemp;

  const compressedJSON = await gzipCompress(jsonValue);
  // Double converting to Base64 so it copies well on Android
  return Buffer.from(compressedJSON.toString("base64")).toString("base64");
}
