import { t } from "../../../utils/I18n";
import { AttackTarget } from "../../exploration/AttackTarget";
import { GameState } from "../../GameState";
import { Spell } from "../../spells/Spell";
import {
  hasTemporaryEffect,
  TemporaryEffects,
} from "../../temporaryeffects/TemporaryEffects";
import {
  AutocastCondition,
  AutocastConditionParameter,
} from "../AutocastCondition";
import { TemporaryEffectAutocastConditionParameterType } from "./parameters/TemporaryEffectAutocastConditionParameterType";

class TemporaryEffectPresentAutocastCondition extends AutocastCondition {
  getId(): string {
    return "IfBuffPresentPlayer";
  }

  getDisplayName(): string {
    return t("If player has buff / debuff");
  }

  getParameters(): AutocastConditionParameter[] {
    return [
      {
        parameterType: new TemporaryEffectAutocastConditionParameterType(),
        name: t("Buff / debuff"),
      },
    ];
  }

  getDisplayExplanation(state: GameState, values: string[]): string {
    return t("If player has buff / debuff *{{buff}}*", {
      buff: TemporaryEffects.getById(values[0]).getDisplayName(),
    });
  }

  isConditionMet(
    state: GameState,
    spell: Spell | undefined,
    values: string[],
  ): boolean {
    return hasTemporaryEffect(state, values[0], AttackTarget.Player);
  }
}

export default new TemporaryEffectPresentAutocastCondition();
