import React, { useContext, useMemo } from "react";
import {
  Platform,
  ScrollView,
  StyleSheet,
  View,
  useWindowDimensions,
} from "react-native";
import { useTheme } from "react-native-paper";
import { ActionArea } from "../../backend/action/ActionAreas";
import { getFavoriteSpells } from "../../backend/spells/Spells";
import Styles from "../utility/Styles";
import { VisibilityContext } from "../utility/VisibilityContext";
import { useGameState } from "../utility/useGameState";
import { FavoriteSpellsContainer } from "./FavoriteSpellsContainer";
import SpellsContainer from "./SpellsContainer";

type Props = {
  favoritesOnBottom: boolean;
};

export function SpellMenuContents(props: { favoritesOnBottom: boolean }) {
  const theme = useTheme();
  const state = useGameState();
  const visible = useContext(VisibilityContext);
  const isBottomMenuEnabled = state.options.enableBottomMenu;
  const { height, width } = useWindowDimensions();

  const heightWithoutBottomMenu =
    height - (isBottomMenuEnabled && width < 640 ? 48 : 0);
  const hasFavorites = getFavoriteSpells(state).length > 0;
  return useMemo(
    () => (
      <View style={{ flex: 1, backgroundColor: theme.colors.background }}>
        <ScrollView
          style={{ flex: 1 }}
          contentContainerStyle={styles.magicContainer}
        >
          <SpellsContainer
            uiKey="SpellMenuContainer"
            area={ActionArea.SPELL_MENU}
            onlyBySchool={props.favoritesOnBottom}
            showWizards={true}
            isVisible={visible}
          />
        </ScrollView>
        {props.favoritesOnBottom && hasFavorites && (
          <View
            style={{
              height: Math.max(
                Math.min(heightWithoutBottomMenu - 472, 270),
                180,
              ),
              borderTopColor: theme.colors.outlineVariant,
              borderTopWidth: 1,
            }}
          >
            {Platform.select({
              default: (
                <View style={[styles.favoritesContainer, Styles.flex]}>
                  <FavoriteSpellsContainer />
                </View>
              ),
              web: (
                <ScrollView contentContainerStyle={styles.favoritesContainer}>
                  <FavoriteSpellsContainer />
                </ScrollView>
              ),
            })}
          </View>
        )}
      </View>
    ),
    [
      theme,
      hasFavorites,
      props.favoritesOnBottom,
      heightWithoutBottomMenu,
      visible,
    ],
  );
}

export function SpellMenu(props: Props) {
  return useMemo(() => {
    return <SpellMenuContents favoritesOnBottom={props.favoritesOnBottom} />;
  }, [props.favoritesOnBottom]);
}

const styles = StyleSheet.create({
  backdrop: {
    position: "absolute",
    height: "100%",
    width: "100%",
    backgroundColor: "#000",
    zIndex: 80,
  },
  backdropTouchable: {
    position: "absolute",
    height: "100%",
    width: "100%",
    zIndex: 90,
  },
  container: {
    justifyContent: "flex-start",
    alignItems: "stretch",
    position: "absolute",
    zIndex: 100,
    height: "100%",
    width: 300,
    right: 0,
    top: 0,
  },
  magicContainer: {
    padding: 8,
  },
  favoritesContainer: {
    paddingHorizontal: 8,
    paddingBottom: 8,
  },
});
