import memoize from "sonic-memoize";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { ActionEffect } from "../action/Action";
import { Identifiable } from "../generic/Identifiable";
import { SpellElement, translateElement } from "../spells/Elements";
import {
  applyTransformationsCached,
  explainTransformationsText,
} from "../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../transformation/TransformationTags";
import { getCurrentSynchroBonuses } from "./Synchro";

export abstract class SynchroBonus implements Identifiable {
  constructor() {}

  getId(): string {
    return this.getElement1() + "-" + this.getElement2();
  }
  abstract getEffect(state: GameState, amount: number): string | undefined;

  protected getBaseBonusEffects(): Record<string, ActionEffect> {
    return {};
  }

  getCachedBaseBonusEffects = memoize(() => this.getBaseBonusEffects());

  abstract getElement1(): SpellElement;
  abstract getElement2(): SpellElement;

  getName(): string {
    return t("Synchro Bonus ({{element1}}+{{element2}})", {
      element1: translateElement(this.getElement1()),
      element2: translateElement(this.getElement2()),
    });
  }

  getTags(): string[] {
    return [];
  }

  isVisible(state: GameState): boolean {
    return true;
  }

  isBeneficial(): boolean {
    return true;
  }

  tagsByEffect: Record<string, string[]> = {};
  getTagsByEffect(key: string, baseEffect: ActionEffect): string[] {
    if (this.tagsByEffect?.[key] == null) {
      this.tagsByEffect[key] = [
        ...this.getTags(),
        this.getId(),
        ...(baseEffect.tags ?? []),
        TransformationTags.SynchroEffect,
      ];
    }
    return this.tagsByEffect[key];
  }

  getBonusEffect(state: GameState, effectId: string, amount?: number): number {
    const baseEffects = this.getCachedBaseBonusEffects();
    const baseEffect = baseEffects[effectId];
    switch (baseEffect.valueType) {
      case TransformationValueType.Divisor:
        return Math.pow(
          applyTransformationsCached(
            this.getTagsByEffect(effectId, baseEffect),
            state,
            baseEffect.value,
          ),
          amount ?? 1,
        );
      case TransformationValueType.Percent:
        return Math.pow(
          applyTransformationsCached(
            this.getTagsByEffect(effectId, baseEffect),
            state,
            baseEffect.value,
          ) + 1.0,
          amount ?? 1,
        );
      case TransformationValueType.Basic:
      default:
        return (
          applyTransformationsCached(
            this.getTagsByEffect(effectId, baseEffect),
            state,
            baseEffect.value,
          ) * (amount ?? 1)
        );
    }
  }

  getBonusEffects(state: GameState, amount?: number): Record<string, number> {
    const baseEffects = this.getCachedBaseBonusEffects();
    const finalEffects: Record<string, number> = {};
    for (let key in baseEffects) {
      const baseEffect = baseEffects[key];
      finalEffects[key] = Math.pow(
        applyTransformationsCached(
          this.getTagsByEffect(key, baseEffect),
          state,
          baseEffect.value,
        ),
        amount ?? 1,
      );
    }
    return finalEffects;
  }

  getExplanationParamsByEffect = memoize((baseEffect: ActionEffect) => ({
    valueType: baseEffect.valueType,
    unit: baseEffect.unit,
  }));

  getBonusEffectExplanations(
    state: GameState,
    amount?: number,
    onlyId?: string,
  ): Record<string, string> {
    const baseEffects = this.getCachedBaseBonusEffects();
    const finalEffects: Record<string, string> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      const transformedEffect = explainTransformationsText(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.getExplanationParamsByEffect(baseEffect),
      );
      if (transformedEffect != "" || amount != 1) {
        finalEffects[key] = t(
          "Results from stacking {{amount}} time(s) the following: {{transformedEffect}}",
          {
            amount: formatNumber(amount ?? 1),
            transformedEffect:
              transformedEffect != ""
                ? transformedEffect
                : t("{{amount}}{{unit}}", {
                    amount: formatNumber(
                      baseEffect.valueType == TransformationValueType.Percent
                        ? 100.0 * baseEffect.value
                        : baseEffect.value,
                      { showDecimals: true },
                    ),
                    unit:
                      baseEffect.valueType == TransformationValueType.Percent
                        ? t("%{{unit}}", { unit: baseEffect.unit })
                        : baseEffect.unit,
                  }),
          },
        );
      }
    }
    return finalEffects;
  }

  getCurrentBonusAmount(state: GameState): number {
    return getCurrentSynchroBonuses(
      state,
      this.getElement1(),
      this.getElement2(),
    );
  }

  getCurrentBonusEffect(state: GameState, effectId: string): number {
    return this.getBonusEffect(
      state,
      effectId,
      this.getCurrentBonusAmount(state),
    );
  }
}
