import { nullthrows, valuesOf } from "../../../utils/CoreUtils";
import { t } from "../../../utils/I18n";
import { Achievement } from "../../Achievement";
import { registerAchievementUnlockerFromFlag } from "../../Achievements";
import { Flag } from "../../Flag";
import { hasFlag, setFlag } from "../../Flags";
import { addGameStateListener } from "../../GameStateListeners";
import { Resource, getResourceAmount, getResourceCap } from "../../Resources";
import { getBuildingAmount, getUsedLand } from "../../buildings/Buildings";
import { essenceCatalyst } from "../../buildings/catalysts/EssenceCatalyst";
import {
  SpellElement,
  getElementLevel,
  getPrimaryElementMaxLevelForRun,
  translateElement,
} from "../../spells/Elements";
import { clearVisibleSpellsCache } from "../../spells/VisibleSpellsCache";
import { RESOURCE_FOR_ELEMENT } from "../../spells/channeling/ChannelingResources";
import { buildEvent } from "../EventMessageBuilder";
import { triggerEvent } from "../Events";
import { EventTag } from "../GameEvent";

export function loadPreTrainingProgression() {
  const channelingUnlockerEventBuilder = buildEvent(
    "channelingUnlocker",
    t("The Power of Magic"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger((state) => {
    state = setFlag(Flag.ChannelingUnlocked)(state);
    state = clearVisibleSpellsCache(state);
    return state;
  });

  channelingUnlockerEventBuilder.message(
    t(`It hasn't been very long since you completed your magical studies, but you still feel like you barely learned anything. But even among the little things you did learn, you remember: after gathering Mana, you are supposed to use it to channel it into elemental Essences...
    
  **You have gained the ability to Channel elemental Essences! You can do so from the Home screen or from the Spell menu.**`),
  );

  const channelingUnlockerEvent = channelingUnlockerEventBuilder.build();

  addGameStateListener("channelingUnlocker", (state) => {
    if (
      !channelingUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.ChannelingUnlocked) &&
      getResourceAmount(state, Resource.Mana) >= 10
    ) {
      return triggerEvent(channelingUnlockerEvent);
    }
  });

  const manaSpoutUnlockerEventBuilder = buildEvent(
    "manaSpoutUnlocker",
    t("Mana Spouts"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.ManaSpoutsUnlocked));

  manaSpoutUnlockerEventBuilder.message(
    t(`You are already pretty tired of gathering Mana on your own. Luckily, you remember something else from your studies: neat little objects that automatically generate it. And you also recall how to make them...
    
**You can now build Mana Spouts!**`),
  );

  const manaSpoutUnlockerEvent = manaSpoutUnlockerEventBuilder.build();

  addGameStateListener("manaSpoutUnlocker", (state) => {
    if (
      !manaSpoutUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.ManaSpoutsUnlocked) &&
      getResourceAmount(state, Resource.AirEssence) > 0 &&
      getResourceAmount(state, Resource.WaterEssence) > 0
    ) {
      return triggerEvent(manaSpoutUnlockerEvent);
    }
  });

  const manaShardUnlockerEventBuilder = buildEvent(
    "manaShardUnlocker",
    t("Mana Capacity"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.ManaShardsUnlocked));

  manaShardUnlockerEventBuilder.message(
    t(`With a more stable Mana income, you feel a little relieved - it is one less problem off your hands. However, your reserves are still not very large. It would be great if you could cast more powerful spells, but that requires more Mana...

You remember your basic lessons, that spoke of earthern shards that could increase this capacity, which can also be easily built. You will need to try that...
    
**You can now build Mana Shards!**`),
  );

  const manaShardUnlockerEvent = manaShardUnlockerEventBuilder.build();

  addGameStateListener("manaShardUnlocker", (state) => {
    if (
      !manaShardUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.ManaShardsUnlocked) &&
      hasFlag(state, Flag.ManaSpoutsUnlocked) &&
      getResourceAmount(state, Resource.EarthEssence) >= 30
    ) {
      return triggerEvent(manaShardUnlockerEvent);
    }
  });

  const storagesUnlockerEventBuilder = buildEvent(
    "storagesUnlocker",
    t("Full of Essence"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.StoragesUnlocked));

  storagesUnlockerEventBuilder.message(
    t(`With more Mana reserves, you have managed to stockpile some elemental Essence. You think of saving some room in your home for magical storage furniture, so you can stockpile even more.

You briefly ask yourself: why would you even want more? But you rid yourself of that thought - with this much Essence, the magical power you have is still very limited... you will surely need to accumulate more.
    
**You can now build Essence Storages, which allow you to hold more elemental Essence at once!**`),
  );

  const storagesUnlockerEvent = storagesUnlockerEventBuilder.build();

  addGameStateListener("storagesUnlocker", (state) => {
    if (
      !storagesUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.StoragesUnlocked) &&
      hasFlag(state, Flag.ManaShardsUnlocked) &&
      getResourceCap(state, Resource.Mana) >= 200 &&
      valuesOf(RESOURCE_FOR_ELEMENT).some(
        (resource) => getResourceAmount(state, resource) >= 100,
      )
    ) {
      return triggerEvent(storagesUnlockerEvent);
    }
  });

  const catalystUnlockerEventBuilder = buildEvent(
    "catalystUnlocker",
    t("Channeling Catalysts"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.CatalystsUnlocked));

  catalystUnlockerEventBuilder.message(
    t(`You are slowly feeling like you are beginning to master Channeling, but something bothers you. You get so little Essence for your efforts.

Once more, you recall the words of your teachers at your school. *"A strong, capable wizard has an elaborate home full of magical furniture."* You remember one more such piece of furniture: *Essence Catalysts*, which use Fire to improve Channeling efficiency.

You contemplate giving that a try...
    
**You can now build Essence Catalysts!**`),
  );

  const catalystUnlockerEvent = catalystUnlockerEventBuilder.build();

  addGameStateListener("catalystUnlocker", (state) => {
    if (
      !catalystUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.CatalystsUnlocked) &&
      hasFlag(state, Flag.StoragesUnlocked) &&
      getResourceAmount(state, Resource.FireEssence) >= 110
    ) {
      return triggerEvent(catalystUnlockerEvent);
    }
  });

  const studyUnlockerEventBuilder = buildEvent(
    "studyUnlocker",
    t("Must Keep Learning"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.StudyUnlocked));

  studyUnlockerEventBuilder.message(
    t(`The Essence Catalyst is up and running! You feel a little proud of it - you notice right away that your channeling spells have become stronger.

Now that you are starting to put what you were meant to have learned from the school into practice, you realize that there is just so much you don't know. You wish you had paid more attention. But at the very least, now that you are starting to see its application, you feel like it would be worthwhile to spend some effort studying it...

You never liked studying - but you finally understand! You even feel like calling yourself a Magic Researcher. You laugh a little at your own thoughts. Why not?
    
**You can now Study the various Elements! Studying earns you experience in the Elements, which eventually increases their level, unlocking new spells. You can Study from the Study screen!**`),
  );

  const studyUnlockerEvent = studyUnlockerEventBuilder.build();

  addGameStateListener("studyUnlocker", (state) => {
    if (
      !studyUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.StudyUnlocked) &&
      hasFlag(state, Flag.CatalystsUnlocked) &&
      getBuildingAmount(state, essenceCatalyst) > 0
    ) {
      return triggerEvent(studyUnlockerEvent);
    }
  });

  const transmuteUnlockerEventBuilder = buildEvent(
    "transmuteUnlocker",
    t("Transmutation"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.TransmutationUnlocked));

  transmuteUnlockerEventBuilder.message(
    t(`As you hone your skills, you recall another way to use elemental Essences. By using magic, you can reshape them into solid objects of great power. It was called *Transmutation* magic. That was it! It has to be how you would make the Philosopher's Stone, isn't it? You avidly pull your notes from the school.

However, your excitement vanishes when you realize most of them are still unintelligible. You turn back to the beginning page. There's a diagram here - but this one feels like it might be simple enough. You remember not understanding heads nor tails of the explanation at the time, but it feels like now that you have finally put some things into practice, your understanding has grown...
    
**You can now cast Transmutation spells! Transmutation spells allow you to convert elemental Essences and other materials into various items. You can do so from the Transmutation screen!**`),
  );

  const transmuteUnlockerEvent = transmuteUnlockerEventBuilder.build();

  addGameStateListener("transmuteUnlocker", (state) => {
    if (
      state.run.primaryElement != null &&
      !transmuteUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.TransmutationUnlocked) &&
      hasFlag(state, Flag.StudyUnlocked) &&
      getPrimaryElementMaxLevelForRun(state) >= 2
    ) {
      return triggerEvent(transmuteUnlockerEvent);
    }
  });

  const wizardsUnlockerEventBuilder = buildEvent(
    "wizardsUnlocker",
    t("A Strange Visitor"),
    [EventTag.MAIN_QUEST],
  );

  wizardsUnlockerEventBuilder.message(
    t(`*Knock, knock.* Someone is at the door of your home. You decide to open it, and meet face to face with a woman. From her attire, she appears to be a wizard. But something feels a little off about her. Her robes are somewhat worn down, and she is oddly carrying a leather bag strapped to her shoulder.
    
*"Wizard for hire from the Wizards' Guild"*, she exclaims, while giving you a paper. You skim through it very quickly. It appears to be a resume listing her skills. It looks impressive indeed, almost too good to be true. You look back at her. She continues: "You only need to tell me what to do, and I will do it for you!"

She must have followed you from the short trip you just did to sell your items...`),
  );
  wizardsUnlockerEventBuilder
    .message(
      t(`"As a special offer, I will join you entirely *for free*!" As she says this, she decides to come inside your home. You are shocked - you didn't agree to anything yet! You are ready to kick her out. But before you reply, you pause and think about it some more. Having someone else helping you cast all those spells would certainly be a big help. Even more so if for free.
    
You ask her to channel some Water, and then you notice her struggling. Beads of sweat start forming on her forehead as she tries to focus to show you her supposed skills. Eventually, she comes through, although she appears to be pretty exhausted. She surprisingly appears to be even less skilled than you are... *no wonder* the offer was actually free.`),
    )
    .option(t("Continue"), { transform: setFlag(Flag.WizardsUnlocked) }, "end");
  wizardsUnlockerEventBuilder
    .message(
      t(`Nevertheless, you help her get set up on a corner of your home. She appears quite excited and thanks you. You know that paper was a complete lie, but the idea still stands: if you are going to create or find the Philosopher's Stone, more helping hands - of any kind - could end up being key to getting there.
    
And maybe it is even worthwhile to have more than one extra pair...

**You have unlocked the ability to direct a team of Wizards to help you cast spells of various kinds! You can do so from the Wizards screen. In addition, you can now build Wizard Chambers, which allow you to hire more Wizards from the Guild!**`),
    )
    .tag("end")
    .end();

  const wizardsUnlockerEvent = wizardsUnlockerEventBuilder.build();

  addGameStateListener("wizardsUnlocker", (state) => {
    if (
      !wizardsUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.WizardsUnlocked) &&
      hasFlag(state, Flag.TransmutationUnlocked) &&
      getResourceAmount(state, Resource.Coins) >= 500
    ) {
      return triggerEvent(wizardsUnlockerEvent);
    }
  });

  const marketUnlockerEventBuilder = buildEvent(
    "marketUnlocker",
    t("Start of a Business"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.SaleStallsUnlocked));

  marketUnlockerEventBuilder.message(
    t(`You have already gone to the town so many times to sell your items. It is a bit of a trip. It would be much more convenient if instead of you having to go there, customers could simply *come* instead and buy your items...
    
You decide to ask the town hall about this. The town officer tells you that you are, in fact, allowed to do so if you wish - and they will even spread the word so that people know about it! She quickly corrects herself - they will spread the word *a little*, at least until your "store" becomes more famous... You decide to give it a try and see if it leads anywhere - having a solid source of income means you can devote your attention to your ultimate goal.

**You can now build Market Stalls to automatically sell items - and as a bonus, they will sell them for higher than their normal sale price!**`),
  );

  const marketUnlockerEvent = marketUnlockerEventBuilder.build();

  addGameStateListener("marketUnlocker", (state) => {
    if (
      !marketUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.SaleStallsUnlocked) &&
      hasFlag(state, Flag.WizardsUnlocked) &&
      getResourceAmount(state, Resource.Coins) >= 1000
    ) {
      return triggerEvent(marketUnlockerEvent);
    }
  });

  const researchersUnlockerEventBuilder = buildEvent(
    "researchersUnlocker",
    t("A Helping Hand"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.ResearchersUnlocked));

  researchersUnlockerEventBuilder.message(
    t(`The new member of your house grabs your attention. "Hey! I've noticed you have been attempting to study very hard. But studying stinks, doesn't it?" She wrinkles her nose in a funny way. "Nowadays, you're meant to get others to do the studying for you! ...Although I think it might cost some :coins:."
    
Getting others to do the studying? You are not very surprised *she* is saying this. But - you recognize that with the growing amount of things to do, it might be a good idea. Perhaps with a specially built furniture and some money, you might just be able to find people who can help...

**You can now build Researcher Desks! Having Researcher Desks will grant you extra Researchers, which can Study the various elements and let you get experience faster!**`),
  );

  const researchersUnlockerEvent = researchersUnlockerEventBuilder.build();

  addGameStateListener("researchersUnlocker", (state) => {
    if (
      !researchersUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.ResearchersUnlocked) &&
      hasFlag(state, Flag.SaleStallsUnlocked) &&
      getResourceAmount(state, Resource.Coins) >= 1500
    ) {
      return triggerEvent(researchersUnlockerEvent);
    }
  });

  const expandHomeUnlockerEventBuilder = buildEvent(
    "expandHomeUnlocker",
    t("A Bigger House?"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.LandPurchaseUnlocked));

  expandHomeUnlockerEventBuilder.message(
    t(`Your home is getting full of furniture. You start wondering if there is a way for you to expand it.
    
Promptly, you go to the town hall again, where the clerk you are informed that you are in luck - there are actually many possibilities for enlargening your :land:Land, and they are quite cheap! But she quickly corrects herself - only *a few of them* are cheap. You thank her for the information regardless. It might be time to start saving up...

**You can now purchase additional land to expand your Home!**`),
  );

  const expandHomeUnlockerEvent = expandHomeUnlockerEventBuilder.build();

  addGameStateListener("expandHomeUnlocker", (state) => {
    if (
      !expandHomeUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.LandPurchaseUnlocked) &&
      hasFlag(state, Flag.ResearchersUnlocked) &&
      getUsedLand(state) >= 120
    ) {
      return triggerEvent(expandHomeUnlockerEvent);
    }
  });

  const explorationUnlockerEventBuilder = buildEvent(
    "explorationUnlocker",
    t("Training"),
    [EventTag.MAIN_QUEST],
  );

  explorationUnlockerEventBuilder.message(
    t(`You receive a familiar letter on your mail today. You sit down and begin to read it. It appears to be from your magic school. It reads:
    
*"Esteemed Student: We would like to extend you an extra free training course on combat. If you are interested, please report to the Tower of /element/ Training. We expect you soon.*"`),
  );
  explorationUnlockerEventBuilder
    .message(
      t(`You are then surprised by a short squeal behind you. It came straight from the first wizard you "hired" - the one that decided to work completely for free. She was standing behind you the entire time, *peeking at the letter* over your shoulder! Angry, you are about to reprimand her, but she exclaims:
    
"*Combat* training? That sounds really exciting! You should definitely participate!" You remind her that your goal is to find or create the Philosopher's Stone and not to get into fights, but she overrides you: "If you are looking for an immensely powerful magical object, don't you think it's going to be hidden and guarded behind enemies that are just as powerful, or worse?"

She might be right...`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = setFlag(Flag.ExplorationUnlocked)(state);
          state = clearVisibleSpellsCache(state);
          return state;
        },
      },
      "end",
    );
  explorationUnlockerEventBuilder
    .message(
      t(`She continues, excited: "So, when are we leaving?" You notice her picking up her robe as if ready to go. You tell her to wait a little. You've made up your mind about this training, but you feel you should at least prepare yourself and get some combat equipment in order first before attending...
    
**You have unlocked Exploration! Venture into various different areas and fight powerful foes. What will you find?**

**You can explore a new dungeon: "Tower of /element/ Training"!**

**You can also now Transmute equipment, including Hand and Body equipment!**`),
    )
    .tag("end")
    .end();

  const explorationUnlockerEvent = explorationUnlockerEventBuilder.build();

  addGameStateListener("explorationUnlocker", (state) => {
    if (
      state.run.primaryElement != null &&
      !explorationUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.ExplorationUnlocked) &&
      hasFlag(state, Flag.LandPurchaseUnlocked) &&
      [
        SpellElement.Fire,
        SpellElement.Earth,
        SpellElement.Water,
        SpellElement.Air,
      ].includes(state.run.primaryElement) &&
      getElementLevel(state, nullthrows(state.run.primaryElement)) >= 6
    ) {
      return triggerEvent(explorationUnlockerEvent, {
        element: translateElement(nullthrows(state.run.primaryElement)),
      });
    }
  });
  loadOtherElementExplorationUnlocker();
}

function loadOtherElementExplorationUnlocker() {
  const explorationUnlockerEventBuilder = buildEvent(
    "explorationUnlockerOtherElement",
    t("Training"),
    [EventTag.MAIN_QUEST],
  );
  explorationUnlockerEventBuilder.message(
    t(`You receive a familiar letter on your mail today. You sit down and begin to read it. It appears to be from your magic school. It reads:
    
*"Esteemed Student: We would like to extend you an extra free training course on combat. If you are interested, please report to the Towers of Training. We expect you soon.*"`),
  );
  explorationUnlockerEventBuilder
    .message(
      t(`You are then surprised by a short squeal behind you. It came straight from the first wizard you "hired" - the one that decided to work completely for free. She was standing behind you the entire time, *peeking at the letter* over your shoulder! Angry, you are about to reprimand her, but she exclaims:
    
"*Combat* training? That sounds really exciting! You should definitely participate!" You remind her that your goal is to find or create the Philosopher's Stone and not to get into fights, but she overrides you: "If you are looking for an immensely powerful magical object, don't you think it's going to be hidden and guarded behind enemies that are just as powerful, or worse?"

She might be right...`),
    )
    .option(
      t("Continue"),
      { transform: setFlag(Flag.ExplorationUnlocked) },
      "end",
    );
  explorationUnlockerEventBuilder
    .message(
      t(`She continues, excited: "So, when are we leaving?" You notice her picking up her robe as if ready to go. You tell her to wait a little. You've made up your mind about this training, but you feel you should at least prepare yourself and get some combat equipment in order first before attending...
    
**You have unlocked Exploration! Venture into various different areas and fight powerful foes. What will you find?**

**You can explore four new dungeons: "Tower of Fire Training", "Tower of Earth Training", "Tower of Water Training" and "Tower of Air Training"!**

**You can also now Transmute equipment, including Hand and Body equipment!**`),
    )
    .tag("end")
    .end();

  const explorationUnlockerEvent = explorationUnlockerEventBuilder.build();

  addGameStateListener("explorationUnlockerOtherElement", (state) => {
    if (
      state.run.primaryElement != null &&
      !explorationUnlockerEvent.hasTriggered(state) &&
      !hasFlag(state, Flag.ExplorationUnlocked) &&
      hasFlag(state, Flag.LandPurchaseUnlocked) &&
      ![
        SpellElement.Fire,
        SpellElement.Earth,
        SpellElement.Water,
        SpellElement.Air,
      ].includes(state.run.primaryElement) &&
      getElementLevel(state, nullthrows(state.run.primaryElement)) >= 5
    ) {
      return triggerEvent(explorationUnlockerEvent, {
        element: translateElement(nullthrows(state.run.primaryElement)),
      });
    }
  });

  registerAchievementUnlockerFromFlag(
    Flag.ExplorationUnlocked,
    Achievement.EXPLORATION,
  );
}
