import { useCallback, useContext, useEffect, useState } from "react";
import {
  GameState,
  INITIAL_STATE,
  StateContextValue,
  createTempState,
} from "../../backend/GameState";
import { clone } from "../../utils/CoreUtils";
import { VisibilityContext } from "./VisibilityContext";

let subscribers: { fn: (state: GameState) => void; stack: string }[] = [];

let initialState: StateContextValue = {
  ...clone(INITIAL_STATE),
  apply: () => {},
  overwrite: () => {},
};

initialState.temp = createTempState();

export function updateGameState(newState: StateContextValue) {
  initialState = newState;
  subscribers.forEach((subscriber) => subscriber.fn(newState));
}

export function useGameState() {
  const [state, setState] = useState<StateContextValue>(initialState);
  const stack = ""; //new Error().stack.split("\n")?.[2];
  const callback = useCallback((newState) => setState(newState), [setState]);
  const visible = useContext(VisibilityContext);

  useEffect(() => {
    if (visible) {
      const subscriber = { fn: callback, stack };
      subscribers.push(subscriber);
      return () => {
        subscribers = subscribers.filter(
          (otherSubscriber) => otherSubscriber != subscriber,
        );
      };
    }
  }, [callback, visible]);

  return state; //useContext(StateContext);
}
