import { SimpleLineIcons } from "@expo/vector-icons";
import React, {
  useCallback,
  useContext,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import { Platform, StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import { StateContextValue } from "../../backend/GameState";
import { ActionArea } from "../../backend/action/ActionAreas";
import { SpellElement } from "../../backend/spells/Elements";
import { Spell } from "../../backend/spells/Spell";
import { getVisibleSpellsByElement } from "../../backend/spells/Spells";
import { clone, keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import CategoryContainer from "../utility/CategoryContainer";
import { GameStateFreezer } from "../utility/GameStateFreezer";
import { SpellElementTag, SpellView } from "../utility/SpellView";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import {
  UIContext,
  UIContextValue,
  useUIStateFromContext,
} from "../utility/UIContext";
import { useGameState } from "../utility/useGameState";
import { FavoriteSpellsContainer } from "./FavoriteSpellsContainer";
import { SpellElementSelector } from "./SpellElementSelector";

function SpellsContainerInside(props: {
  uiKey: string;
  area?: ActionArea;
  onlyBySchool?: boolean;
  showAddToQuickbar?: boolean;
  showWizards?: boolean;
  hideTitle?: boolean;
  dividerColor?: string;
  state: StateContextValue;
  uiContext: UIContextValue;
  isVisible?: boolean;
}) {
  const state = props.state;
  const uiContext = props.uiContext;

  const currentElementKey = props.uiKey + ".currentElement";
  const spellsByElement = getVisibleSpellsByElement(state, props.area);

  const elements = keysOf(spellsByElement);

  const [currentElement, setCurrentElement] = useUIStateFromContext(
    uiContext,
    currentElementKey,
    elements?.[0] ?? SpellElement.Fire,
  );

  useEffect(() => {
    if (!elements.includes(currentElement)) {
      setCurrentElement(elements?.[0]);
    }
  }, [elements.join(",")]);

  const [expandedSpells, setExpandedSpells] = useState<Record<string, boolean>>(
    {},
  );
  const expandedSpellsRef = useRef(expandedSpells);
  expandedSpellsRef.current = expandedSpells;

  const currentElementSpells = spellsByElement?.[currentElement] || [];

  const toggleCollapsed = useCallback((spell: Spell) => {
    const id = spell.getId();
    expandedSpellsRef.current[id] = !expandedSpellsRef?.current?.[id];
    setExpandedSpells(clone(expandedSpellsRef.current));
  }, []);

  const boundToggleCollapsed = useMemo(
    () =>
      Object.fromEntries(
        elements.map((element) => [
          element,
          Object.fromEntries(
            spellsByElement[element].map((spell) => [
              spell.getId(),
              toggleCollapsed.bind(null, spell),
            ]),
          ),
        ]),
      ),
    [spellsByElement],
  );

  const spellActionContainers = useMemo(() => {
    if (!props.showAddToQuickbar) {
      return elements.map((element) => {
        const spellArray = spellsByElement[element];
        return (
          <GameStateFreezer
            key={element}
            isVisible={currentElement === element}
          >
            <View style={currentElement !== element && { display: "none" }}>
              {spellArray.map((spell, idx) => {
                return (
                  <SpellView
                    key={spell.getId()}
                    spell={spell}
                    showAddToQuickbar={props.showAddToQuickbar}
                    showWizards={props.showWizards}
                    collapsed={!expandedSpells?.[spell.getId()]}
                    toggleCollapsed={
                      boundToggleCollapsed[element][spell.getId()]
                    }
                    isVisible={props.isVisible && currentElement === element}
                  />
                );
              })}
            </View>
          </GameStateFreezer>
        );
      });
    }
    return currentElementSpells.map((spell, idx) => {
      return (
        <SpellView
          key={spell.getId()}
          spell={spell}
          showAddToQuickbar={props.showAddToQuickbar}
          showWizards={props.showWizards}
          collapsed={!expandedSpells?.[spell.getId()]}
          toggleCollapsed={boundToggleCollapsed[currentElement][spell.getId()]}
          isVisible={props.isVisible}
        />
      );
    });
  }, [
    !props.showAddToQuickbar ? spellsByElement : currentElementSpells,
    JSON.stringify(expandedSpells),
    boundToggleCollapsed,
    currentElement,
    props.isVisible,
    props.showWizards,
  ]);

  const spellElementPicker = useMemo(
    () => (
      <SpellElementSelector
        onPress={(value) => setCurrentElement(value as SpellElement)}
        style={styles.picker}
        selected={currentElement}
        elements={elements}
        dividerColor={props.dividerColor}
      />
    ),
    [
      setCurrentElement,
      currentElement,
      JSON.stringify(elements),
      props.dividerColor,
    ],
  );

  const currentElementSpellsString = currentElementSpells
    .map((spell) => spell.getId())
    .join(",");

  const hasAllSpellsExpanded = useMemo(() => {
    return !currentElementSpells.some(
      (spell) => !(expandedSpells?.[spell.getId()] ?? false),
    );
  }, [expandedSpells, currentElementSpellsString]);

  const expandCollapseAll = useCallback(() => {
    let expandedSomething = false;
    currentElementSpells.forEach((spell) => {
      const spellId = spell.getId();
      if (!expandedSpells?.[spellId]) {
        expandedSomething = true;
        expandedSpells[spellId] = true;
      }
    });
    if (!expandedSomething) {
      currentElementSpells.forEach((spell) => {
        delete expandedSpells[spell.getId()];
      });
    }
    setExpandedSpells(clone(expandedSpells));
  }, [expandedSpells, currentElementSpellsString]);

  const theme = useTheme();

  return useMemo(() => {
    return (
      <View>
        {!props.hideTitle && (
          <View
            accessible={true}
            accessibilityRole="header"
            style={styles.emptyTitleContainer}
          >
            <SimpleLineIcons
              name="magic-wand"
              size={24}
              style={styles.magicWand}
              color={theme.colors.onBackground}
            />
            <Text h2 accessibilityRole="none">
              {t("Cast Spell")}
            </Text>
          </View>
        )}
        {!props.onlyBySchool && (
          <View>
            <FavoriteSpellsContainer isVisible={props.isVisible} />
          </View>
        )}
        {elements.length > 0 ? (
          <CategoryContainer
            title={t("By Element")}
            dividerColor={props.dividerColor}
          >
            <View style={styles.schoolRowContainer}>
              <View style={styles.tagPickerContainer}>
                {spellElementPicker}
              </View>
              <View style={Styles.flexCenter}>
                <Button compact={true} onPress={expandCollapseAll}>
                  {hasAllSpellsExpanded ? t("Collapse all") : t("Expand all")}
                </Button>
                <View style={Styles.mt8}>
                  <SpellElementTag element={currentElement} />
                </View>
              </View>
            </View>
            <View style={styles.schoolContainer}>{spellActionContainers}</View>
          </CategoryContainer>
        ) : (
          <View style={styles.noSpellsContainer}>
            <Text style={styles.noSpellsText}>{t("No spells yet!")}</Text>
          </View>
        )}
      </View>
    );
  }, [
    props.hideTitle,
    currentElement,
    setCurrentElement,
    spellActionContainers,
    props.isVisible,
    spellElementPicker,
    expandCollapseAll,
    props.onlyBySchool,
    props.dividerColor,
    elements.length,
    hasAllSpellsExpanded,
    theme,
  ]);
}

export default function SpellsContainer(props: {
  uiKey: string;
  area: ActionArea;
  hideRecent?: boolean;
  showAddToQuickbar?: boolean;
  showWizards?: boolean;
  isVisible?: boolean;
  hideTitle?: boolean;
  onlyBySchool?: boolean;
  dividerColor?: string;
}) {
  const state = useGameState();
  const uiContext = useContext(UIContext);

  return useMemo(
    () => (
      <GameStateFreezer isVisible={props.isVisible}>
        <SpellsContainerInside state={state} uiContext={uiContext} {...props} />
      </GameStateFreezer>
    ),
    [
      props.area,
      props.hideRecent,
      props.showAddToQuickbar,
      props.onlyBySchool,
      props.showWizards,
      props.isVisible !== false ? state : false,
      props.isVisible !== false ? uiContext : false,
      props.isVisible !== false ? props.dividerColor : false,
    ],
  );
}

const styles = StyleSheet.create({
  schoolContainer: { marginTop: 8 },
  spellsContainer: { marginTop: 8 },
  schoolButton: { marginVertical: 4 },
  magicWand: { marginRight: 8 },
  schoolTitleContainer: {
    flexDirection: "row",
    alignItems: "center",
    flex: 1,
    justifyContent: "center",
  },
  emptyTitleContainer: {
    flexDirection: "row",
    justifyContent: "center",
  },
  titleContainer: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  schoolTitleTextContainer: {},
  picker: {
    flex: 1,
    width: 144,
  },
  schoolRowContainer: {
    marginTop: 8,
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "space-between",
  },
  tagPickerContainer: {
    width: 180,
  },
  buttonTitle: {
    textAlign: "center",
    paddingVertical: 1,
    ...Platform.select({
      android: {
        fontFamily: "sans-serif-medium",
      },
      default: {
        fontSize: 18,
      },
    }),
    fontSize: 12,
  },
  noSpellsText: {
    fontStyle: "italic",
  },
  noSpellsContainer: {
    marginTop: 16,
  },
  nuxText: {
    fontStyle: "italic",
  },
});
