import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  getResourceInfo,
  getSortedResourceKeys,
} from "../../backend/Resources";
import { formatNumber } from "../../utils/FormattingUtils";
import { Text } from "./Text";
import { GameIcon } from "./icons/GameIcon";
import { useGameState } from "./useGameState";

const EPSILON = 1e-6;

function ResourceItem(props: {
  resource: Resource;
  current: number;
  cap: number;
}) {
  const resourceInfo = getResourceInfo(props.resource);
  const isCapped = props.current >= props.cap - EPSILON;
  const icon = useMemo(
    () => <GameIcon icon={resourceInfo.icon} style={styles.icon} small />,
    [resourceInfo.icon],
  );
  const theme = useTheme();
  const textStyle = useMemo(
    () => isCapped && { color: theme.colors.tertiary },
    [isCapped, theme],
  );
  const textContents = `${formatNumber(props.current, {
    showDecimals: true,
  })}/${formatNumber(props.cap)}`;
  const text = useMemo(
    () => (
      <Text style={textStyle} doNotScaleFonts={true}>
        {textContents}
      </Text>
    ),
    [textContents, textStyle],
  );
  return (
    <View style={styles.item} accessible={true}>
      {icon}
      {text}
    </View>
  );
}

const ResourceItemMemo = React.memo(ResourceItem);

export function BottomResourceViewer(props: { isVisible?: boolean }) {
  const state = useGameState();
  const resourceKeys = getSortedResourceKeys(state) as Array<Resource>;
  const rows = useMemo(
    () =>
      resourceKeys.map((resourceName) => {
        if (resourceName == Resource.Mana) {
          return null;
        }
        return (
          <ResourceItemMemo
            resource={resourceName}
            current={getResourceAmount(state, resourceName)}
            cap={getResourceCap(state, resourceName)}
            key={resourceName}
          />
        );
      }),
    [props.isVisible !== false ? state : false],
  );
  return <View style={styles.container}>{rows}</View>;
}

const styles = StyleSheet.create({
  container: {
    flexDirection: "row",
    flexWrap: "wrap",
    justifyContent: "space-around",
  },
  item: {
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "center",
    width: 106,
  },
  inlineRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginRight: 2,
  },
});
