import React from "react";
import { StyleSheet, View } from "react-native";
import { EquipmentSetButtons } from "./inventory/EquipmentSetButtons";
import EquippedItems from "./inventory/EquippedItems";
import EquippedPouch from "./inventory/EquippedPouch";
import InventoryItems from "./inventory/InventoryItems";
import Styles from "./utility/Styles";

export default function Inventory() {
  return (
    <View style={styles.container}>
      <View style={styles.fullBleedPadding}>
        <View style={styles.equippedItemsContainer}>
          <EquippedItems />
        </View>
        <View style={Styles.mb16}>
          <EquippedPouch />
        </View>
        <View style={styles.equipmentSetButtonsRow}>
          <EquipmentSetButtons />
        </View>
      </View>
      <InventoryItems isFullBleed={true} showExtraActions={true} />
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  topRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: 16,
    height: 48,
  },
  arrowBack: {
    marginRight: 8,
  },
  equippedItemsContainer: {
    marginBottom: 8,
  },
  row: {
    justifyContent: "flex-start",
  },
  topButtons: {
    flexDirection: "row",
    alignItems: "center",
  },
  craftButton: {
    marginLeft: 16,
  },
  fullBleedPadding: {
    paddingHorizontal: 8,
    marginTop: 8,
  },
  equipmentSetButtonsRow: {
    marginBottom: 16,
  },
  smallButtonTitle: {
    fontSize: 12,
  },
});
