import { t } from "../../../../utils/I18n";
import { Achievement } from "../../../Achievement";
import { registerAchievementUnlockerFromStoryline } from "../../../Achievements";
import { setDungeonFloorCompleted } from "../../../exploration/dungeons/Dungeons";
import TrainingTowerAir from "../../../exploration/dungeons/training/TrainingTowerAir";
import TrainingTowerEarth from "../../../exploration/dungeons/training/TrainingTowerEarth";
import TrainingTowerFire from "../../../exploration/dungeons/training/TrainingTowerFire";
import TrainingTowerWater from "../../../exploration/dungeons/training/TrainingTowerWater";
import { addEnemyDeathListener } from "../../../exploration/Exploration";
import { Flag } from "../../../Flag";
import { hasFlag, setFlag } from "../../../Flags";
import { HealthPotion } from "../../../items/equipment/pouch/HealthPotion";
import { addToInventory } from "../../../items/Inventory";
import { paper } from "../../../items/transmute/materials/Paper";
import { grantResource, Resource } from "../../../Resources";
import { registerPostRetirementListener } from "../../../Retire";
import { RESOURCE_FOR_ELEMENT } from "../../../spells/channeling/ChannelingResources";
import { getUnlockedElements } from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

const storyline = new Storyline(
  "zoomThroughTraining",
  t("Zoom Through Training"),
  160,
  10,
  t("Defeat the Head Instructor within 5 in-game minutes after starting a run"),
  t(
    "Start new retirements with all game progress until the end of the Training Towers completed; +14.9K Max of all Essences; +9K Max:monstium:; +15K Max:coins:; +20:land:; Begin new retirements with 100 :paper:Paper, full :monstium: and storage of all Essences, and 3 :healthPotion:Health Potion",
  ),
  (state) => true,
);

export function loadZoomThroughTraining() {
  Storylines.register(storyline);
  const eventBuilder = buildEvent(
    "zoomThroughTraining",
    t("(Storyline) Zoom Through Training"),
    [EventTag.STORYLINE],
  ).setStorylineIds(storyline.getId());

  eventBuilder
    .message(
      t(
        `Your victory in combat against the Head Instructor marks the end of your combat training, but you really feel like you have no need for it anymore. Everything so far felt like a complete breeze.`,
      ),
    )
    .option(t("Continue"), {
      transform: (state) => {
        state = storyline.complete(state);
        return state;
      },
    });
  eventBuilder.message(
    t(`You write a few more notes in your diary. Everything you found in that house once you moved in has helped you so much - and with this last bit of information, if anyone else comes after you, they'll be able to get going... *very* quickly.

**You have completed the "Zoom Through Training" Storyline! In future retirements, you will begin with all the unlocks you have obtained up to this point already unlocked. This means you will not need to explore the Training Towers and will be able to start directly from the next area! But you will also obtain many more bonuses: a flat +14.9K bonus to all your Essence storing capacity, +15K Max:coins: and +9000 Max:monstium:, as well as an extra +20:land:! You will also begin with 100 :paper:Paper in your inventory, and 3 :healthPotion:Health Potion so you don't need to look for their materials. And to top it all off, your Essence and Monstium storage will start full!**`),
  );
  const event = eventBuilder.build();

  addEnemyDeathListener(event.getId() + "Triggerer", (state) => {
    if (
      !event.hasTriggered(state) &&
      !storyline.isCompleted(state) &&
      getSecondsPlayed(state) <= 60 * 5 &&
      hasFlag(state, Flag.HeadInstructorDefeated)
    ) {
      return triggerEvent(event)(state);
    }
    return state;
  });

  registerPostRetirementListener("zoomThroughTraining", (state) => {
    if (storyline.isBonusActive(state)) {
      const flagsToActivate = [
        Flag.ChannelingUnlocked,
        Flag.ManaSpoutsUnlocked,
        Flag.ManaShardsUnlocked,
        Flag.StoragesUnlocked,
        Flag.CatalystsUnlocked,
        Flag.StudyUnlocked,
        Flag.TransmutationUnlocked,
        Flag.WizardsUnlocked,
        Flag.SaleStallsUnlocked,
        Flag.ResearchersUnlocked,
        Flag.LandPurchaseUnlocked,
        Flag.ExplorationUnlocked,
        Flag.RestOfTrainingUnlocked,
        Flag.EnhancementsUnlocked,
        Flag.SewersUnlocked,
      ];
      for (let flag of flagsToActivate) {
        state = setFlag(flag)(state);
      }

      const dungeonsToClear = [
        TrainingTowerAir,
        TrainingTowerEarth,
        TrainingTowerFire,
        TrainingTowerWater,
      ];
      for (let dungeon of dungeonsToClear) {
        state = setDungeonFloorCompleted(state, dungeon.getId());
      }

      state = grantResource(Resource.Monstium, Infinity)(state);
      const unlockedElements = getUnlockedElements(state);
      for (let element of unlockedElements) {
        state = grantResource(RESOURCE_FOR_ELEMENT[element], Infinity)(state);
      }
      state = addToInventory({ itemId: paper.getId(), params: {} }, 100, state);
      state = addToInventory(
        { itemId: HealthPotion.getId(), params: {} },
        3,
        state,
      );
    }
    return state;
  });

  registerTransformation(
    [
      ...Object.values(RESOURCE_FOR_ELEMENT).map((resource) => [
        resource,
        TransformationTags.Cap,
      ]),
    ],
    "zoomThroughTrainingStorage",
    storyline.getName(),
    TransformationType.Addition,
    (state, params) => (storyline.isBonusActive(state) ? 14900 : 0),
  );

  registerTransformation(
    [[Resource.Coins, TransformationTags.Cap]],
    "zoomThroughTrainingCoins",
    storyline.getName(),
    TransformationType.Addition,
    (state, params) => (storyline.isBonusActive(state) ? 15000 : 0),
  );

  registerTransformation(
    [[Resource.Monstium, TransformationTags.Cap]],
    "zoomThroughTrainingMonstium",
    storyline.getName(),
    TransformationType.Addition,
    (state, params) => (storyline.isBonusActive(state) ? 9000 : 0),
  );

  registerTransformation(
    [[TransformationTags.Land, TransformationTags.Cap]],
    "zoomThroughTrainingLand",
    storyline.getName(),
    TransformationType.Addition,
    (state, params) => (storyline.isBonusActive(state) ? 20 : 0),
  );

  registerAchievementUnlockerFromStoryline(
    storyline,
    Achievement.ZOOM_THROUGH_TRAINING,
  );
}
