import { t } from "../../../../utils/I18n";
import { trainingTowers } from "../../../exploration/dungeons/training/TrainingTowers";
import { getCurrentDungeonFloor } from "../../../exploration/Exploration";
import { ExplorationStatus } from "../../../exploration/ExplorationStatus";
import { getExplorationStatus } from "../../../exploration/ExplorationUtils";
import { HealthPotion } from "../../../items/equipment/pouch/HealthPotion";
import {
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../../../items/Inventory";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { registerRandomEventTrigger } from "../../autorandom/RandomEventTrigger";
import { buildEvent } from "../../EventMessageBuilder";
import { EventTag } from "../../GameEvent";

const alchemyClassStoryline = new Storyline(
  "alchemyClass",
  t("Alchemy Class"),
  110,
  10,
  (state) => t("Explore the Training Towers"),
  t("Item {{item}} restores an extra +15:hp:", {
    item: HealthPotion.getBaseName(HealthPotion.getDefaultParams()),
  }),
  (_state) => true,
);

export function loadAlchemyClass() {
  Storylines.register(alchemyClassStoryline);

  const alchemyClassEventBuilder = buildEvent(
    "alchemyClass",
    t("(Storyline) Alchemy Class"),
    [EventTag.STORYLINE],
  ).setStorylineIds(alchemyClassStoryline.getId());

  alchemyClassEventBuilder.message(
    t(`In the Tower of Training, you find a room that looks exactly like a classroom. You are not surprised to find such rooms - after all, this place is connected to your old magic school. You peek inside. The classroom appears to be empty, so you decide to go in.
    
You look around the room. It is definitely something related to Alchemy, as evidenced by the giant cauldron in the front. It appears this place has been used recently.

Then you hear a faint voice from behind you: *"Is someone there?"*`),
  );
  alchemyClassEventBuilder.message(
    t(
      `You turn around and find a lady of advanced age. She is dressed in the same way your teachers at your school were, but you never met her. You introduce yourself and tell her that you are doing the special combat training at this tower.
        
"Admirable," she says. "Do you know how to defeat difficult adversaries? Perhaps the most important items in that situation are potions. Even if you cannot tell others to help you drink them, you do not need Mana to do so, so you can stay focused on using powerful spells. At the same time, among those, Health Potions are *particularly* important - they let you stay alive and heal your wounds."

You intently listen to her lecture.`,
    ),
  );

  alchemyClassEventBuilder
    .message(
      t(`"It just so happens that these potions are my specialty. I know how to make them particularly strong, but if you'd like me to teach you, you'll first have to show me that you have enough experience to understand my explanation."
  
She then asks you: "Show me that you can do this. Bring me 6 Health Potions, and I will consider telling you about it."

She starts turning back on you, but right before she does so, she interrupts herself to say one last thing: "Oh, and - in case you didn't know - you can transmute Health Potions by having Water Lv6 or higher."`),
    )
    .option(
      t("Hand over the potions (6 Health Potion)"),
      {
        transform: (state) => {
          state = removeFromInventoryWithAnyParams(HealthPotion, 6, state);
          return state;
        },
        isEnabled: (state) => {
          return getTotalAmountOfItem(HealthPotion, state) >= 6;
        },
      },
      "end",
    )
    .option(t("Maybe later"))
    .end();
  alchemyClassEventBuilder
    .message(
      t(
        `You hand over the potions to the teacher. She is delighted: "Wonderful! You must have really learned Alchemy well at school." You are tempted to tell her that you only just learned how to make these potions recently, after finishing the school, but you keep it to yourself.
        
"I will now tell you the secret technique to making Health Potions," she says. You begin taking notes in a frenzy as she goes through detailed explanations of how and when to combine the ingredients of the potion, as well as how to pre-treat the Wildgrass.

When she is finally done, you thank her for the lesson and head back out.`,
      ),
    )
    .option(
      "Continue",
      {
        transform: (state) => alchemyClassStoryline.complete(state),
      },
      "final",
    )
    .tag("end")
    .end();
  alchemyClassEventBuilder
    .message(
      t(
        `When you get home again, you are ready to put it into practice. Except when you look at your notes again, they don't make too much sense. Your hired Wizard looks at you with interest and glances over your notes, before exclaiming: "I can't make heads or tails of what you wrote!".

You have a small argument with her over the rude comment, but she is admittedly right - you feel disappointed in yourself. Perhaps in the future someone else can understand these? You get back to doing what you were doing before.

**You have completed the "Alchemy Class" Storyline! In future runs, Health Potions will heal an extra 15:hp:!**`,
      ),
    )
    .tag("final");

  const alchemyClassEvent = alchemyClassEventBuilder.build();

  registerTransformation(
    [[TransformationTags.ItemEffect, HealthPotion.getId()]],
    "alchemyClass",
    alchemyClassStoryline.getName(),
    TransformationType.Addition,
    (state) => (alchemyClassStoryline.isBonusActive(state) ? 15 : 0),
  );

  registerRandomEventTrigger(
    alchemyClassEvent,
    40,
    (state) =>
      !alchemyClassStoryline.isCompleted(state) &&
      !alchemyClassEvent.hasTriggered(state) &&
      getExplorationStatus(state) != ExplorationStatus.None &&
      trainingTowers.getDungeonFloors().includes(getCurrentDungeonFloor(state)),
  );
}

export { alchemyClassStoryline };
