import { AttackTarget } from "../../../exploration/AttackTarget";
import { GameState } from "../../../GameState";
import { TemporaryEffect } from "../../../temporaryeffects/TemporaryEffect";
import {
  clearTemporaryEffect,
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { ItemParams } from "../../Item";
import { PouchItem } from "../PouchItem";

// Note: This class expects an action effect called "duration"
// It will pass all other item effects as params to the temporary effect
export abstract class BuffingPouchItem extends PouchItem {
  abstract getTemporaryEffect(): TemporaryEffect;

  getId(): string {
    return this.getTemporaryEffect().getId();
  }

  getPicture() {
    return this.getTemporaryEffect().getIcon();
  }

  getBaseName(params: ItemParams): string {
    return this.getTemporaryEffect().getDisplayName();
  }

  getTarget() {
    return AttackTarget.Player;
  }

  doPouchItemEffect(state: GameState, params: ItemParams): GameState {
    const { duration, ...other } = this.getItemEffects(state, params);
    state = grantTemporaryEffect(this.getTemporaryEffect().getId(), duration, {
      target: this.getTarget(),
      params: other,
    })(state);
    return state;
  }

  doCleanup(state: GameState, params: ItemParams): GameState {
    state = super.doCleanup(state, params);
    state = clearTemporaryEffect(
      this.getTemporaryEffect().getId(),
      this.getTarget(),
    )(state);
    return state;
  }

  shouldAutoUse(state: GameState, params: ItemParams): boolean {
    return !hasTemporaryEffect(
      state,
      this.getTemporaryEffect().getId(),
      this.getTarget(),
    );
  }
}
