import React, { useContext, useEffect, useMemo, useState } from "react";
import { StyleSheet, View } from "react-native";
import { retire } from "../backend/Retire";
import { Storylines } from "../backend/storylines/Storylines";
import { formatNumber } from "../utils/FormattingUtils";
import { Markdown } from "./utility/Markdown";
import { Text } from "./utility/Text";

import { useTheme } from "react-native-paper";
import { createHypotheticalState } from "../backend/GameState";
import { getEventMessageById } from "../backend/events/Events";
import {
  mplIsTooHigh,
  mplIsTooLow,
} from "../backend/exploration/dungeons/Dungeons";
import {
  getElementLevel,
  getMaxOfMaxPrimaryElementLevels,
  getPrimaryElementMaxLevelForRun,
  getUnlockedElements,
  grantStartingExp,
  maximumElementLevelExpMultiplier,
  maximumElementLevelProductionMultiplierDx,
  startingExpForElement,
  translateElement,
} from "../backend/spells/Elements";
import { t } from "../utils/I18n";
import { Button } from "./utility/Button";
import { VisibilityContext } from "./utility/VisibilityContext";
import { useGameState } from "./utility/useGameState";

export default function Retirement() {
  const state = useGameState();

  const allElementLevels = getUnlockedElements(state).map((eachElement) =>
    getElementLevel(state, eachElement),
  );
  const allStorylines = Storylines.getAll()
    .filter((storyline) => storyline.isCompletedThisRun(state))
    .map((storyline) => storyline.id)
    .join();
  const modifiedState = useMemo(() => {
    let retiredState = retire(createHypotheticalState(state), true);
    retiredState = grantStartingExp(retiredState);
    return retiredState;
  }, [JSON.stringify(allElementLevels), allStorylines]);
  const newExpMultiplier = maximumElementLevelExpMultiplier(modifiedState);
  const newProductionMultiplier =
    maximumElementLevelProductionMultiplierDx(modifiedState);
  const oldExpMultiplier = maximumElementLevelExpMultiplier(state);
  const oldProductionMultiplier =
    maximumElementLevelProductionMultiplierDx(state);
  const expDeltaPct =
    ((newExpMultiplier - oldExpMultiplier) * 100.0) / oldExpMultiplier;
  const productionDeltaPct =
    ((newProductionMultiplier - oldProductionMultiplier) * 100.0) /
    oldProductionMultiplier;
  const stuckOnBoss = false; //TBD isStuckOnBoss(state);

  const newStorylines = Storylines.getAll().filter((storyline) => {
    return storyline.isCompletedThisRun(state);
  });

  let storylineBonusMessage = "";
  if (newStorylines.length > 0) {
    storylineBonusMessage = t(
      `
      
Additionally, in this retirement, you have completed the following Storylines, which will be fully activated after retirement:
    
{{storylineList}}`,
      {
        storylineList: newStorylines
          .map((storyline) =>
            t("* **{{storylineName}}**: {{storylineBonus}}", {
              storylineName: storyline.getName(),
              storylineBonus: storyline.getBonusDescription(state),
            }),
          )
          .join(t("\n")),
      },
    );
  }

  const startingExp = startingExpForElement(
    modifiedState,
    state.run.primaryElement,
  );
  const startingLevel = getElementLevel(
    modifiedState,
    state.run.primaryElement,
  );

  const startingExpSecondary =
    state.run.secondaryElement != null
      ? startingExpForElement(modifiedState, state.run.secondaryElement)
      : 0;
  const startingLevelSecondary =
    state.run.secondaryElement != null
      ? getElementLevel(modifiedState, state.run.secondaryElement)
      : 0;

  const retirementEffects = t(
    `**If you retire now, your element exp gain multiplier will become {{newExp}}x (+{{expDeltaPct}}%) and your channeling, production and loot multiplier will become {{newProduction}}x (+{{productionDeltaPct}}%). You will also begin all subsequent retirements with {{startingExp}} {{element}} exp (meaning it will start on at least Lv{{startingLevel}}){{secondaryElementText}}.**`,
    {
      newExp: formatNumber(newExpMultiplier, { showDecimals: true }),
      expDeltaPct: formatNumber(expDeltaPct, {
        showDecimals: true,
      }),
      newProduction: formatNumber(newProductionMultiplier, {
        showDecimals: true,
      }),
      productionDeltaPct: formatNumber(productionDeltaPct, {
        showDecimals: true,
      }),
      startingExp: formatNumber(startingExp),
      startingLevel: formatNumber(startingLevel),
      element: translateElement(state.run.primaryElement),
      secondaryElementText:
        state.run.secondaryElement != null
          ? t(
              " and {{startingExp}} {{element}} exp (meaning it will start on at least Lv{{startingLevel}})",
              {
                startingExp: formatNumber(startingExpSecondary),
                startingLevel: formatNumber(startingLevelSecondary),
                element: translateElement(state.run.secondaryElement),
              },
            )
          : "",
    },
  );

  const text = t(
    `You have gone through a lot, even if it wasn't quite enough. You feel tired and contemplate giving up and retiring from your quest.

If you retire, **you will reset the game.** Everything will revert back to its initial state, except:
* Any bonuses you obtained from "(Storyline)" events will remain;
* You will gain a boost to exp gain, building and channeling production, and loot based on the maximum level you have ever attained on your primary element (MPL), as well as on the sum of the MPLs you have attained on each element;
* You will additionally start with some exp on elements for which your MPL is higher than 1;
* If you have obtained any :timepiece:Time Pieces from time spent away from the game, these will also remain.

When you retire, you will be able to choose a new primary element.

{{retirementEffects}}{{storylineBonusMessage}}

*Do you want to retire?*`,
    { retirementEffects, storylineBonusMessage },
  );

  const [hasClicked, setHasClicked] = useState(false);
  const visible = useContext(VisibilityContext);
  useEffect(() => {
    if (!visible) {
      setHasClicked(false);
    }
  }, [visible]);
  const showOngoingEventsWarning = Object.values(
    state.run.eventOccurrences,
  ).some((event) => {
    return (
      event.currentMessageId &&
      !getEventMessageById(event.currentMessageId).isTerminal()
    );
  });
  const showMPLTooLowWarning =
    mplIsTooLow(state) &&
    getPrimaryElementMaxLevelForRun(state) >
      getMaxOfMaxPrimaryElementLevels(state) + 4;
  const showMPLTooHighWarning = mplIsTooHigh(state);
  const theme = useTheme();

  return (
    <View style={styles.container}>
      <Text h1>{t("Retirement")}</Text>
      <View style={styles.explanation}>
        <Markdown>{text}</Markdown>
      </View>
      {showMPLTooLowWarning && (
        <View style={styles.warning}>
          <Text style={{ fontStyle: "italic" }}>
            {t(`It looks like your multipliers might be too low for your current
progression in Exploration, and your retirement bonus seems
reasonably high. This might be a good time to retire!`)}
          </Text>
        </View>
      )}
      {showMPLTooHighWarning && (
        <View style={styles.warning}>
          <Text style={{ color: theme.colors.error }}>
            {t(`Warning: Your multipliers seem to be very high for your current
progression in Exploration. You might benefit more by making more
progress in Exploration. Are you sure you want to retire?`)}
          </Text>
        </View>
      )}
      {stuckOnBoss && (
        <View style={styles.warning}>
          <Text style={{ color: theme.colors.error }}>
            {t(`Warning: It looks like you could be stuck on a boss. If you cannot
defeat a boss, and you have already tried retiring once to defeat it
without success, you may possibly have better results by thinking
harder about how to defeat the boss rather than starting over again.
Are you sure you want to retire?`)}
          </Text>
        </View>
      )}
      {showOngoingEventsWarning && (
        <View style={styles.warning}>
          <Text style={{ color: theme.colors.error }}>
            {t(
              `Warning: You still have ongoing events! Are you sure you want to retire?`,
            )}
          </Text>
        </View>
      )}
      <View style={styles.buttonContainer}>
        {!hasClicked && (
          <Button onPress={() => setHasClicked(true)}>{t("Retire")}</Button>
        )}
        {hasClicked && (
          <Button
            onPress={() => {
              state.apply((state) => retire(state, false));
            }}
          >
            {t("Are you sure? Press again to confirm")}
          </Button>
        )}
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  explanation: {
    marginTop: 16,
  },
  buttonContainer: {
    marginTop: 16,
  },
  warning: {
    marginTop: 8,
  },
});
