import { fromEntries, keysOf } from "../../../utils/CoreUtils";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { cacheByArbitraryVersion, cacheByTickCount } from "../../Caching";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  grantResource,
} from "../../Resources";
import { ActionEffect, DoActionArgs } from "../../action/Action";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { TransformationTags } from "../../transformation/TransformationTags";
import {
  SpellElement,
  getElementExpByUsage,
  getUnlockedElements,
} from "../Elements";
import { Spell } from "../Spell";
import { RESOURCE_FOR_ELEMENT } from "./ChannelingResources";

export abstract class BasicChannelingSpellBase extends Spell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    const parentAreas = super.getAreas();
    parentAreas.HOME = [ActionSubcategories.CHANNELING];
    return parentAreas;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.CHANNELING;
  }

  getBaseManaCost(state: GameState): number {
    return Math.floor(
      getResourceCap(state, Resource.Mana) * this.getManaCostProportion(),
    );
  }

  getBaseEssenceEfficiency() {
    return 1.0;
  }

  getManaCostProportion() {
    return 0.1;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      essenceEfficiency: {
        value: this.getBaseEssenceEfficiency(),
        tags: [TransformationTags.ChannelingEfficiency],
      },
    };
  }

  getEssencesToGrant(): Resource | "all" {
    return RESOURCE_FOR_ELEMENT[this.getElement()];
  }

  getEssenceAmountToGrant(state: GameState): number {
    return cacheByTickCount(
      state,
      "essenceAmountToGrant" + this.getId(),
      (state) => {
        const effects = this.getActionEffects(state);
        const amount = this.getBaseManaCost(state) * effects.essenceEfficiency;
        return amount;
      },
    );
  }

  getDisplayEffect(state: GameState): string {
    const resource = this.getEssencesToGrant();
    const amount = this.getEssenceAmountToGrant(state);
    if (resource === "all") {
      const resourceTexts: string[] = [];
      for (let element of getUnlockedElements(state)) {
        resourceTexts.push(
          t("+{{amount}}{{resourceIcon}}", {
            amount: formatNumber(amount),
            resourceIcon:
              ":" + RESOURCE_FOR_ELEMENT[element].toLowerCase() + ":",
          }),
        );
      }
      return resourceTexts.join(t(", "));
    }
    return t(
      "^+{{amount}}^<{{resourceIcon}}/:mana: calculation: {{explanation}}>{{resourceIcon}}",
      {
        amount: formatNumber(amount),
        resourceIcon: `:${resource.toLowerCase()}:`,
        explanation: this.getActionEffectExplanations(
          state,
          "essenceEfficiency",
        ).essenceEfficiency,
      },
    );
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.ChannelingUnlocked) && super.isVisible(state);
  }

  isEnabled(state: GameState, skipAffordabilityChecks?: boolean): boolean {
    return (
      super.isEnabled(state, skipAffordabilityChecks) &&
      this.isCastingRecommended(state)
    );
  }

  getExpFromUsage(state: GameState): Partial<Record<SpellElement, number>> {
    return cacheByArbitraryVersion(
      state,
      this.getId() + "expFromUsage",
      (state) => {
        const elementRequirements = this.getLevelRequirements();
        return fromEntries(
          keysOf(elementRequirements).map((element) => {
            return [
              element,
              getElementExpByUsage(
                state,
                element,
                elementRequirements[element],
                (this.getCost(state).resources[Resource.Mana] ?? 0) *
                  Math.log10(Math.max(1, this.getEssenceAmountToGrant(state))),
                true,
                this.getTags(),
              ),
            ];
          }),
        );
      },
      (state) => Math.floor(Date.now() / 500),
    );
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    const resource = this.getEssencesToGrant();
    const amount = this.getEssenceAmountToGrant(state);

    if (resource === "all") {
      for (let element of getUnlockedElements(state)) {
        state = grantResource(RESOURCE_FOR_ELEMENT[element], amount)(state);
      }
      return state;
    }

    return grantResource(resource, amount)(state);
  }

  isCastingRecommended(state: GameState): boolean {
    const resource = this.getEssencesToGrant();
    if (resource != "all") {
      return (
        getResourceAmount(state, resource) < getResourceCap(state, resource)
      );
    }
    for (let element of getUnlockedElements(state)) {
      if (
        (resource == "all" || resource == RESOURCE_FOR_ELEMENT[element]) &&
        getResourceAmount(state, RESOURCE_FOR_ELEMENT[element]) <
          getResourceCap(state, RESOURCE_FOR_ELEMENT[element])
      ) {
        return true;
      }
    }
    return false;
  }
}
