import { useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { Surface } from "react-native-paper";
import { Familiars, getFamiliarData } from "../../backend/familiars/Familiar";
import { getTotalAmountOfItem } from "../../backend/items/Inventory";
import { FamiliarItem } from "../../backend/items/consumable/FamiliarItem";
import { keysOf } from "../../utils/CoreUtils";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";
import { ItemDetailsAction } from "./ItemDetailsAction";

function FamiliarRow(props: {
  familiarId: string;
  item: FamiliarItem;
  params: any;
}) {
  const state = useGameState();
  const familiarData = getFamiliarData(state, props.familiarId);
  const familiar = Familiars.getById(familiarData.familiarClassId);
  const familiarLevel = familiar.getLevel(state, props.familiarId);

  const familiarAction = props.item.getFamiliarAction(
    state,
    props.params,
    props.familiarId,
  );
  return (
    <Surface mode="flat" key={props.familiarId} style={styles.rowContainer}>
      <View style={Styles.flexRowSpaceBetween}>
        <Text h4 style={Styles.bold}>
          {t("{{familiarName}} - Lv{{familiarLevel}}", {
            familiarName: familiar.getName(),
            familiarLevel: familiarLevel,
          })}
        </Text>
        <View style={Styles.ml16}>
          <ItemDetailsAction action={familiarAction} />
        </View>
      </View>
    </Surface>
  );
}

export function ItemFamiliarModalContents(props: {
  onClose: () => void;
  item: FamiliarItem;
  params: any;
}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const familiarIds = keysOf(state.run.familiars);
  const amount = getTotalAmountOfItem(props.item, state);

  return useMemo(() => {
    return (
      <View>
        <View style={Styles.mb8}>
          <Text h3>
            {t("Use {{item}} ({{amount}} left) on...", {
              item: props.item.getName(props.params),
              amount: formatNumber(amount),
            })}
          </Text>
        </View>
        {familiarIds.map((familiarId) => (
          <FamiliarRow
            familiarId={familiarId}
            key={familiarId}
            item={props.item}
            params={props.params}
          />
        ))}
        <View style={Styles.mt8}>
          <Button onPress={props.onClose}>{t("Close")}</Button>
        </View>
      </View>
    );
  }, [familiarIds.join(), props.item, props.params, amount]);
}

const styles = StyleSheet.create({
  rowContainer: {
    marginVertical: 8,
    padding: 8,
    borderRadius: 8,
  },
});
