import React, { useMemo, useState } from "react";
import { StyleSheet, View } from "react-native";
import { Card, List } from "react-native-paper";
import { clearAllCaches } from "../backend/Caching";
import { createHypotheticalState } from "../backend/GameState";
import {
  getBlazingSpeedLevel,
  getNewGamePlusCount,
  getSheerPowerLevel,
  getStorylineOracleLevel,
  getWorldDifficultyLevel,
} from "../backend/NewGamePlus";
import { ActionArea } from "../backend/action/ActionAreas";
import { getCurrentChallenge } from "../backend/challenges/Challenges";
import { getCurrentCombatClass } from "../backend/classes/CombatClasses";
import {
  getMarkdownTextForSpell,
  getMarkdownTextForSpellEmpowerment,
  getMarkdownTextForTransmutationSpell,
} from "../backend/events/progression/LevelUp";
import { getVisibleTransmutationSpells } from "../backend/items/transmute/TransmutationSpell";
import {
  SpellElement,
  getElementExp,
  getElementLevel,
  getMaxElementLevel,
  getMaxPrimaryElementLevel,
  getPrimaryElements,
  getRankForLevel,
  getTotalExpRequiredForLevel,
  getUnlockedElements,
  maximumElementLevelExpMultiplier,
  maximumElementLevelProductionMultiplierDx,
  translateElement,
} from "../backend/spells/Elements";
import { Spell } from "../backend/spells/Spell";
import { getVisibleSpellsForElement } from "../backend/spells/Spells";
import {
  STORYLINE_STATUS_ORDER,
  StorylineStatus,
  translateStorylineStatus,
} from "../backend/storylines/Storyline";
import {
  getSortedStorylines,
  getTotalStorylineCount,
} from "../backend/storylines/Storylines";
import { getResearcherExpPerSecForElements } from "../backend/study/Study";
import { fromEntries, keysOf } from "../utils/CoreUtils";
import { formatNumber, formatTimeLong } from "../utils/FormattingUtils";
import { t } from "../utils/I18n";
import { Card as UtilityCard } from "./utility/Card";
import EndingHistoryContainer from "./utility/EndingHistoryContainer";
import { GameStateFreezer } from "./utility/GameStateFreezer";
import { Markdown } from "./utility/Markdown";
import { Picker } from "./utility/Picker";
import StorylinesContainer from "./utility/StorylinesContainer";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

function StatsSingleElement(props: { element: SpellElement }) {
  const element = props.element;
  const state = useGameState();
  const allElementLevels = fromEntries(
    getUnlockedElements(state).map((eachElement) => [
      eachElement,
      getElementLevel(state, eachElement),
    ]),
  );

  const researchEstimate = getResearcherExpPerSecForElements(state);

  const currentRank = getRankForLevel(allElementLevels[element]);
  const newSpellsText = useMemo(() => {
    const thisLevelSpells = getVisibleSpellsForElement(
      state,
      element,
      ActionArea.SPELL_MENU,
    );
    const thisLevelCrafts = getVisibleTransmutationSpells(state);
    const thisLevelEmpowerments = thisLevelSpells.filter((spell) =>
      spell.isEmpoweringPossible(state),
    );
    const currentElementLevel = getElementLevel(state, element);
    let nextUnlockLevelAdd = 0;
    let foundNewSpellsOrItems = false;
    let extraSpells: Spell[] = [];
    let extraCrafts: Spell[] = [];
    let extraEmpowerments: Spell[] = [];
    let fakeState = createHypotheticalState(state);
    while (
      !foundNewSpellsOrItems &&
      currentElementLevel + nextUnlockLevelAdd < getMaxElementLevel(state)
    ) {
      nextUnlockLevelAdd += 1;
      fakeState.run.elementExperience[element] = getTotalExpRequiredForLevel(
        fakeState,
        getElementLevel(state, element) + nextUnlockLevelAdd,
        element,
      );
      fakeState = clearAllCaches(fakeState);
      const nextLevelSpells = getVisibleSpellsForElement(
        fakeState,
        element,
        ActionArea.SPELL_MENU,
      );
      const nextLevelTransmutations = getVisibleTransmutationSpells(fakeState);
      const nextLevelEmpowerments = nextLevelSpells.filter((spell) =>
        spell.isEmpoweringPossible(fakeState),
      );
      extraSpells = nextLevelSpells.filter(
        (spell) => !thisLevelSpells.includes(spell),
      );
      extraCrafts = nextLevelTransmutations.filter(
        (item) => !thisLevelCrafts.includes(item),
      );
      extraEmpowerments = nextLevelEmpowerments.filter(
        (spell) => !thisLevelEmpowerments.includes(spell),
      );
      if (
        extraSpells.length > 0 ||
        extraCrafts.length > 0 ||
        extraEmpowerments.length > 0
      ) {
        foundNewSpellsOrItems = true;
      }
    }
    const newSpells = extraSpells.map(getMarkdownTextForSpell);
    const newTransmutations = extraCrafts.map(
      getMarkdownTextForTransmutationSpell.bind(null, state),
    );
    const newEmpowerments = extraEmpowerments.map(
      getMarkdownTextForSpellEmpowerment,
    );
    const newUnlocks = [...newSpells, ...newEmpowerments, ...newTransmutations];

    return newUnlocks.length > 0
      ? t(
          `Your next unlock is at level {{level}}:
      
{{unlocks}}`,
          {
            level: nextUnlockLevelAdd + currentElementLevel,
            unlocks: newUnlocks.join(t("\n")),
          },
        )
      : t(
          `If you only level up this element, you will not unlock anything new!`,
        );
  }, [JSON.stringify(allElementLevels)]);

  const nextLevelRequiredExp =
    getTotalExpRequiredForLevel(
      state,
      getElementLevel(state, element) + 1,
      element,
    ) - getElementExp(state, element);

  return (
    <View>
      <View style={styles.row}>
        <Text>{t("Rank:")}</Text>
        <Text style={styles.valueText}>{currentRank}</Text>
      </View>
      <View style={styles.row}>
        <Text>{t("Level:")}</Text>
        <Text style={styles.valueText}>{getElementLevel(state, element)}</Text>
      </View>
      <View style={styles.row}>
        <Text>{t("To Next Level:")}</Text>
        <Text style={styles.valueText}>
          {formatNumber(nextLevelRequiredExp)}
        </Text>
      </View>
      <View style={styles.row}>
        <Text>{t("To Next Level (Time):")}</Text>
        <Text style={styles.valueText}>
          {(researchEstimate?.[element] ?? 0) == 0 ||
          nextLevelRequiredExp == Infinity
            ? ""
            : formatTimeLong(
                moment.duration(
                  (1000 * nextLevelRequiredExp) /
                    (researchEstimate?.[element] ?? 0),
                ),
              )}
        </Text>
      </View>
      <View style={styles.row}>
        <Text style={{ flex: 1 }}>
          {t("Maximum Primary Level in previous runs (MPL):")}
        </Text>
        <Text style={styles.valueText}>
          {formatNumber(getMaxPrimaryElementLevel(state, element))}
        </Text>
      </View>
      <View style={styles.insideBlock}>
        <Markdown>{newSpellsText}</Markdown>
      </View>
    </View>
  );
}

const StatsElementViewerElement = React.memo(
  (props: { element: SpellElement }) => {
    const state = useGameState();

    const element = props.element;
    const level = getElementLevel(state, element);
    const title = t("{{element}} Lv{{level}}{{primary}} - {{rank}}", {
      element: translateElement(element),
      level: formatNumber(level),
      primary: getPrimaryElements(state).includes(element)
        ? t(" (Primary)")
        : "",
      rank: getRankForLevel(level),
    });

    return useMemo(
      () => (
        <View key={element}>
          <List.Accordion title={title}>
            <View style={styles.elementElementContainer}>
              <StatsSingleElement element={element} />
            </View>
          </List.Accordion>
        </View>
      ),
      [title, element],
    );
  },
);

function StatsElementViewer() {
  const state = useGameState();

  const elements = getUnlockedElements(state);

  const perElement = elements.map((element, index) => {
    return <StatsElementViewerElement key={element} element={element} />;
  });

  return <View>{perElement}</View>;
}

function StorylineViewer() {
  const state = useGameState();
  const allStorylines = getSortedStorylines();
  const completedStorylines = allStorylines.filter((storyline) =>
    storyline.isCompleted(state),
  );
  const storylineCount = getTotalStorylineCount();

  const storylineStatusMap: Partial<Record<StorylineStatus, number>> = {};
  for (let storyline of allStorylines) {
    const status = storyline.getStatus(state);
    storylineStatusMap[status] = (storylineStatusMap[status] ?? 0) + 1;
  }

  const storylineStatusSet = new Set(keysOf(storylineStatusMap));

  const [storylineStatus, setStorylineStatus] = useState<string>(
    storylineStatusSet.has(StorylineStatus.HINT_REVEALED)
      ? StorylineStatus.HINT_REVEALED
      : storylineStatusSet.has(StorylineStatus.UNLOCKED)
      ? StorylineStatus.UNLOCKED
      : storylineStatusSet.has(StorylineStatus.COMPLETED)
      ? StorylineStatus.COMPLETED
      : StorylineStatus.BONUS_ACTIVE,
  );

  const storylineStatuses = Array.from(storylineStatusSet)
    .sort((a, b) => {
      const indexA = STORYLINE_STATUS_ORDER.findIndex((status) => status == a);
      const indexB = STORYLINE_STATUS_ORDER.findIndex((status) => status == b);
      return indexA - indexB;
    })
    .filter((status) => status != StorylineStatus.HIDDEN);

  const statusPicker = useMemo(() => {
    return (
      <Picker
        onValueChange={setStorylineStatus}
        selectedValue={storylineStatus}
        data={[...storylineStatuses].map((status: string) => ({
          label: t("{{storylineStatus}} ({{amount}})", {
            storylineStatus: translateStorylineStatus(
              status as StorylineStatus,
            ),
            amount: formatNumber(storylineStatusMap[status] ?? 0),
          }),
          value: status,
          key: status,
        }))}
      />
    );
  }, [
    setStorylineStatus,
    storylineStatus,
    JSON.stringify(storylineStatuses),
    JSON.stringify(storylineStatusMap),
  ]);

  return useMemo(
    () => (
      <View>
        <View style={styles.row}>
          <Text>{t("Total Completed Storylines:")}</Text>
          <Text style={styles.valueText}>
            {t("{{completedStorylines}} / {{allStorylines}}", {
              completedStorylines: formatNumber(completedStorylines.length),
              allStorylines: formatNumber(storylineCount),
            })}
          </Text>
        </View>
        <View style={styles.row}>
          <Text>{t("Completion Percentage:")}</Text>
          <Text style={styles.valueText}>
            {t("{{completedStorylinesPct}}%", {
              completedStorylinesPct: formatNumber(
                completedStorylines.length > storylineCount
                  ? 100 + (completedStorylines.length - storylineCount)
                  : (100 * completedStorylines.length) / storylineCount,
              ),
            })}
          </Text>
        </View>
        <View style={[Styles.mt16, Styles.flexRowAlignCenter]}>
          <Text>{t("Filter Storylines:")}</Text>
          <View style={[Styles.ml8, { width: 180 }]}>{statusPicker}</View>
        </View>
        <View style={Styles.mt8}>
          {STORYLINE_STATUS_ORDER.map((status) => (
            <View
              style={storylineStatus != status && { display: "none" }}
              key={status}
            >
              <GameStateFreezer isVisible={storylineStatus == status}>
                <StorylinesContainer onlyStorylineStatus={status} />
              </GameStateFreezer>
            </View>
          ))}
        </View>
      </View>
    ),
    [
      completedStorylines.length,
      allStorylines.length,
      statusPicker,
      storylineStatus,
    ],
  );
}

function StatsOtherViewer() {
  const state = useGameState();
  const thisRunDuration = moment.duration(state.run.secondsPlayed, "seconds");
  const thisWorldDuration = moment.duration(
    state.world.secondsPlayed,
    "seconds",
  );
  const totalDuration = moment.duration(state.global?.secondsPlayed, "seconds");
  const thisRunActiveDuration = formatTimeLong(
    moment.duration(state.run.activeSecondsPlayed, "seconds"),
  );
  const thisWorldActiveDuration = formatTimeLong(
    moment.duration(state.world.activeSecondsPlayed, "seconds"),
  );
  const totalActiveDuration = formatTimeLong(
    moment.duration(state.global.activeSecondsPlayed, "seconds"),
  );
  const thisRunTimePieces = state.run.timePiecesConsumed;
  const thisWorldTimePieces = state.world.timePiecesConsumed;
  const totalTimePieces = state.global.timePiecesConsumed;
  const thisRunRealWorldDuration = moment.duration(
    Date.now() - state.run.startTimestamp ?? 0,
  );
  const thisWorldRealWorldDuration = moment.duration(
    Date.now() - state.world.startTimestamp ?? 0,
  );
  const totalRealWorldDuration = moment.duration(
    Date.now() - state.global.startTimestamp ?? 0,
  );

  const newGamePlusCount = getNewGamePlusCount(state);
  const sheerPowerLevel = getSheerPowerLevel(state);
  const storylineOracleLevel = getStorylineOracleLevel(state);
  const blazingSpeedLevel = getBlazingSpeedLevel(state);
  const worldDifficultyLevel = getWorldDifficultyLevel(state);

  const expMultiplier = maximumElementLevelExpMultiplier(state);
  const productionMultiplier = maximumElementLevelProductionMultiplierDx(state);

  const currentChallenge = getCurrentChallenge(state);

  const primaryElement = getPrimaryElements(state)
    .map((element) => translateElement(element))
    .join(t(", "));

  const primaryElementRow = useMemo(
    () => (
      <View style={styles.row}>
        <Text style={styles.captionText} numberOfLines={2}>
          {t("Primary element:")}
        </Text>
        <Text style={[styles.valueText, styles.valueTextLong]}>
          {primaryElement}
        </Text>
      </View>
    ),
    [primaryElement],
  );

  const inGameTimeTotalRow = useMemo(
    () => (
      <View style={styles.row}>
        <Text style={styles.captionText} numberOfLines={2}>
          {t("In-game time played (total):")}
        </Text>
        <Text
          style={[styles.valueText, styles.valueTextLong]}
          numberOfLines={3}
        >
          {formatTimeLong(totalDuration)}
        </Text>
      </View>
    ),
    [formatTimeLong(totalDuration)],
  );

  const inGameTimeThisWorldRow = useMemo(
    () =>
      newGamePlusCount > 0 && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("In-game time played (this World):")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {formatTimeLong(thisWorldDuration)}
          </Text>
        </View>
      ),
    [newGamePlusCount, formatTimeLong(thisWorldDuration)],
  );

  const inGameTimeThisRunRow = useMemo(
    () => (
      <View style={styles.row}>
        <Text style={styles.captionText} numberOfLines={3}>
          {t("In-game time played (this run):")}
        </Text>
        <Text style={[styles.valueText, styles.valueTextLong]}>
          {formatTimeLong(thisRunDuration)}
        </Text>
      </View>
    ),
    [formatTimeLong(thisRunDuration)],
  );

  const activeDurationTotalRow = useMemo(
    () =>
      totalActiveDuration != null && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Real world time played (total):")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {totalActiveDuration}
          </Text>
        </View>
      ),
    [totalActiveDuration],
  );

  const activeDurationThisWorldRow = useMemo(
    () =>
      newGamePlusCount > 0 && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Real world time played (this World):")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {thisWorldActiveDuration}
          </Text>
        </View>
      ),
    [newGamePlusCount, thisWorldActiveDuration],
  );

  const timePiecesThisRunRow = useMemo(
    () =>
      thisRunTimePieces > 0 && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Time Pieces consumed (this run):")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {formatNumber(thisRunTimePieces)}
          </Text>
        </View>
      ),
    [thisRunTimePieces],
  );

  const timePiecesTotalRow = useMemo(
    () =>
      totalTimePieces > 0 && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Time Pieces consumed (total):")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {formatNumber(totalTimePieces)}
          </Text>
        </View>
      ),
    [totalTimePieces],
  );

  const timePiecesThisWorldRow = useMemo(
    () =>
      newGamePlusCount > 0 &&
      thisWorldTimePieces > 0 && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Time Pieces consumed (this World):")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {formatNumber(thisWorldTimePieces)}
          </Text>
        </View>
      ),
    [newGamePlusCount, thisWorldTimePieces],
  );

  const activeDurationThisRunRow = useMemo(
    () => (
      <View style={styles.row}>
        <Text style={styles.captionText} numberOfLines={3}>
          {t("Real world time played (this run):")}
        </Text>
        <Text style={[styles.valueText, styles.valueTextLong]}>
          {thisRunActiveDuration}
        </Text>
      </View>
    ),
    [thisRunActiveDuration],
  );

  const realWorldDurationTotalRow = useMemo(
    () => (
      <View style={styles.row}>
        <Text style={styles.captionText} numberOfLines={3}>
          {t("Real world time since starting the game:")}
        </Text>
        <Text style={[styles.valueText, styles.valueTextLong]}>
          {formatTimeLong(totalRealWorldDuration)}
        </Text>
      </View>
    ),
    [formatTimeLong(totalRealWorldDuration)],
  );

  const realWorldDurationThisWorldRow = useMemo(
    () =>
      newGamePlusCount > 0 && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Real world time since starting this World:")}
          </Text>
          <Text style={[styles.valueText, styles.valueTextLong]}>
            {formatTimeLong(thisWorldRealWorldDuration)}
          </Text>
        </View>
      ),
    [newGamePlusCount, formatTimeLong(thisWorldRealWorldDuration)],
  );

  const realWorldDurationThisRunRow = useMemo(
    () => (
      <View style={styles.row}>
        <Text style={styles.captionText} numberOfLines={3}>
          {t("Real world time since starting this run:")}
        </Text>
        <Text style={[styles.valueText, styles.valueTextLong]}>
          {formatTimeLong(thisRunRealWorldDuration)}
        </Text>
      </View>
    ),
    [formatTimeLong(thisRunRealWorldDuration)],
  );

  const combatClassRow = useMemo(
    () =>
      getCurrentCombatClass(state) != null && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Class:")}
          </Text>
          <Text style={styles.valueText}>
            {getCurrentCombatClass(state).getDisplayName()}
          </Text>
        </View>
      ),
    [getCurrentCombatClass(state)],
  );

  const challengeRow = useMemo(
    () =>
      getCurrentChallenge(state) != null && (
        <View style={styles.row}>
          <Text style={styles.captionText} numberOfLines={3}>
            {t("Challenge:")}
          </Text>
          <Text style={[styles.valueText]}>
            {getCurrentChallenge(state).getName()}
          </Text>
        </View>
      ),
    [getCurrentChallenge(state)],
  );

  const newGamePlusRows = useMemo(
    () =>
      newGamePlusCount > 0 && (
        <>
          <View style={styles.row}>
            <Text style={styles.captionText} numberOfLines={2}>
              {t("Other Worlds visited:")}
            </Text>
            <Text style={[styles.valueText, styles.valueTextLong]}>
              {formatNumber(newGamePlusCount)}
            </Text>
          </View>
          <View style={styles.row}>
            <Text style={styles.captionText} numberOfLines={2}>
              {t("World modifiers:")}
            </Text>
            <Text style={[styles.valueText, styles.valueTextLong]}>
              {t(
                "{{sheerPower}}/{{storylineOracle}}/{{blazingSpeed}} - D{{worldDifficulty}}",
                {
                  sheerPower: formatNumber(sheerPowerLevel),
                  storylineOracle: formatNumber(storylineOracleLevel),
                  blazingSpeed: formatNumber(blazingSpeedLevel),
                  worldDifficulty: formatNumber(worldDifficultyLevel),
                },
              )}
            </Text>
          </View>
        </>
      ),
    [
      newGamePlusCount,
      sheerPowerLevel,
      storylineOracleLevel,
      blazingSpeedLevel,
      worldDifficultyLevel,
    ],
  );

  const retirementRows = useMemo(
    () => (
      <>
        <View style={styles.row}>
          <Text>{t("Total times retired:")}</Text>
          <Text style={styles.valueText}>{state.world.totalTimesResetted}</Text>
        </View>
        <View style={styles.row}>
          <Text>{t("Global experience multiplier:")}</Text>
          <Text style={styles.valueText}>
            {formatNumber(expMultiplier, { showDecimals: true })}x
          </Text>
        </View>
        <View style={styles.row}>
          <Text>{t("Global production multiplier:")}</Text>
          <Text style={styles.valueText}>
            {formatNumber(productionMultiplier, { showDecimals: true })}x
          </Text>
        </View>
      </>
    ),
    [state.world.totalTimesResetted, expMultiplier, productionMultiplier],
  );

  return useMemo(
    () => (
      <View>
        {challengeRow}
        {primaryElementRow}
        {combatClassRow}
        {inGameTimeTotalRow}
        {inGameTimeThisWorldRow}
        {inGameTimeThisRunRow}
        {activeDurationTotalRow}
        {activeDurationThisWorldRow}
        {activeDurationThisRunRow}
        {timePiecesThisRunRow}
        {timePiecesThisWorldRow}
        {timePiecesTotalRow}
        {realWorldDurationTotalRow}
        {realWorldDurationThisWorldRow}
        {realWorldDurationThisRunRow}
        {newGamePlusRows}
        {retirementRows}
      </View>
    ),
    [
      challengeRow,
      primaryElementRow,
      combatClassRow,
      inGameTimeTotalRow,
      inGameTimeThisWorldRow,
      inGameTimeThisRunRow,
      activeDurationTotalRow,
      activeDurationThisWorldRow,
      activeDurationThisRunRow,
      timePiecesTotalRow,
      timePiecesThisWorldRow,
      timePiecesThisRunRow,
      realWorldDurationTotalRow,
      realWorldDurationThisWorldRow,
      realWorldDurationThisRunRow,
      newGamePlusRows,
      retirementRows,
    ],
  );
}

export default function Stats() {
  const state = useGameState();
  const showEndings = state.global.endingsHistory.length > 0;

  return useMemo(
    () => (
      <View style={styles.container}>
        <UtilityCard>
          <Card.Title title={t("Stats")} accessibilityRole="header" />
          <Card.Content>
            <StatsOtherViewer />
          </Card.Content>
        </UtilityCard>
        <UtilityCard style={Styles.mt16}>
          <Card.Title title={t("Elements")} accessibilityRole="header" />
          <Card.Content>
            <StatsElementViewer />
          </Card.Content>
        </UtilityCard>
        <UtilityCard style={Styles.mt16}>
          <Card.Title title={t("Storylines")} accessibilityRole="header" />
          <Card.Content>
            <StorylineViewer />
          </Card.Content>
        </UtilityCard>
        {showEndings && (
          <UtilityCard style={Styles.mt16}>
            <Card.Title
              title={t("Ending History")}
              accessibilityRole="header"
            />
            <Card.Content>
              <EndingHistoryContainer />
            </Card.Content>
          </UtilityCard>
        )}
        {/* {hasSecrets && (
          <Card>
            <RNECard.Title accessibilityRole="header">Secrets</RNECard.Title>
            <SecretsContainer secretType={SecretType.Basic} />
          </Card>
        )} */}
      </View>
    ),
    [showEndings],
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  elementElementContainer: {
    margin: 8,
    marginBottom: 24,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    marginBottom: 4,
  },
  insideBlock: {
    marginTop: 16,
  },
  valueText: {
    minWidth: 60,
    textAlign: "right",
    marginLeft: 8,
  },
  valueTextLong: {
    minWidth: 112,
  },
  captionText: {
    flex: 1,
  },
});
