const boldHotkeyLetter = (title: string, hotkey: string, theme: any) => {
  const hotkeyPos = title.toLowerCase().indexOf(hotkey.toLowerCase());
  let firstHalf = "";
  let lastHalf = "";

  if (hotkeyPos == 0) {
    lastHalf = title.substring(1);
  } else {
    firstHalf = title.substring(0, hotkeyPos);
    lastHalf = title.substring(hotkeyPos + 1);
  }

  return (
    <Text>
      {firstHalf}
      <Text style={[styles.hotkeyText, { color: theme.colors.tertiary }]}>
        {hotkeyPos == 0 ? hotkey.toUpperCase() : hotkey.toLowerCase()}
      </Text>
      {lastHalf}
    </Text>
  );
};

const MenuComponent = (
  props: {
    title: string;
    section: string;
    onPress: () => void;
    showBadge: boolean;
    showFavoriteIcon: boolean;
    favorited: boolean;
    onPressFavorite: () => void;
    hotkey?: string;
    wide?: boolean;
    children: React.ReactNode;
  } & any,
) => {
  const { title, onPress, showBadge, ...others } = props;
  const theme = useTheme();
  const visible = useContext(VisibilityContext);
  const onKeyboard = useCallback(
    (ev: KeyboardEvent) => {
      if (!visible || props.hotkey == null) {
        return;
      }

      if (
        ((!ev.shiftKey && !ev.ctrlKey) || (ev.shiftKey && ev.ctrlKey)) &&
        ev.key.toLowerCase() == props.hotkey.toLowerCase()
      ) {
        onPress();
      }
    },
    [visible, onPress],
  );
  const favoriteButton = useMemo(
    () => (
      <TouchableOpacity
        accessibilityLabel={
          props.favorited ? t("Unpin Section") : t("Pin Section")
        }
        onPress={props.onPressFavorite}
        style={styles.favoriteButton}
      >
        <Ionicons
          name={props.favorited ? "heart" : "heart-outline"}
          color={theme.colors.onBackground}
          size={14}
        />
      </TouchableOpacity>
    ),
    [props.favorited, props.onPressFavorite, theme],
  );
  return (
    <View>
      <KeyboardListener callback={onKeyboard} />
      <TouchableOpacity
        onPress={onPress}
        Component={Platform.select<any>({
          android: TouchableNativeFeedback,
          default: TouchableOpacity,
        })}
        accessibilityLabel={title}
        accessibilityRole="button"
        accessible={true}
        focusable={true}
        {...others}
        style={[
          styles.menuComponentTouch,
          { borderColor: theme.colors.outlineVariant },
          props.wide && { width: 96 },
        ]}
      >
        <View style={styles.menuComponentButton}>
          <GameIcon
            /* @ts-expect-error accessibilityHidden is supported in web */
            focusable={false}
            accessibilityHidden={true}
            icon={props.section.toLowerCase()}
          />
          {showBadge && <Badge style={styles.notificationBadge} size={8} />}
          {props.showFavoriteIcon && favoriteButton}
          <View style={Styles.mt2}>
            {Platform.OS === "web" && props.hotkey ? (
              <Text
                style={[styles.menuItemText, props.wide && { fontSize: 13 }]}
                numberOfLines={1}
              >
                {boldHotkeyLetter(title, props.hotkey, theme)}
              </Text>
            ) : (
              <Text
                style={[styles.menuItemText, props.wide && { fontSize: 13 }]}
                numberOfLines={1}
              >
                {title}
              </Text>
            )}
          </View>
        </View>
      </TouchableOpacity>
    </View>
  );
};

export function MenuContents(props: {
  visible: boolean;
  pinnedSections: string[];
  wide: boolean;
  onPressFavorite: (section: string) => void;
  onNavigationPress: (section: string) => void;
}) {
  const state = useGameState();
  const stateRef = useRef(state);

  const layout = useLayoutType();

  const favoritesFull =
    props.pinnedSections.length >= layout.activeContentTabs - 1;

  const menuComponent = useCallback(
    (sectionKey: string, sectionTitle: string, menuProps?: any) => {
      const sectionInfo = getSectionInfo(sectionKey);
      return (
        <MenuComponent
          title={sectionTitle}
          section={sectionKey}
          key={sectionKey}
          onPress={() => props.onNavigationPress(sectionKey)}
          onPressFavorite={() => props.onPressFavorite(sectionKey)}
          favorited={props.pinnedSections.includes(sectionKey)}
          showFavoriteIcon={
            (props.pinnedSections.includes(sectionKey) || !favoritesFull) &&
            !sectionInfo.fullScreen
          }
          wide={props.wide}
          {...(menuProps ?? {})}
        />
      );
    },
    [
      props.onNavigationPress,
      props.onPressFavorite,
      props.wide,
      props.pinnedSections.join(),
      favoritesFull,
    ],
  );

  const menuItems = getAllMenuItems(state);
  const sectionInfo = getAllSections();
  const badgeCacheKey = sectionInfo
    .map((section) => (section.isBadged?.(state) ? "y" : "n"))
    .join();

  const navigation = useMemo(
    () => (
      <View style={[styles.navigationBlock, props.wide && { width: 300 }]}>
        {menuItems.map((item, idx) =>
          menuComponent(
            item,
            sectionInfo.find((section) => section.id == item)?.name,
            {
              topDivider: idx == 0,
              showBadge: sectionInfo
                .find((section) => section.id == item)
                ?.isBadged?.(state),
              hotkey: sectionInfo.find((section) => section.id == item)?.hotkey,
            },
          ),
        )}
      </View>
    ),
    [
      props.onNavigationPress,
      props.onPressFavorite,
      menuComponent,
      menuItems.join(","),
      badgeCacheKey,
    ],
  );

  const hasTemporaryEffects = getTemporaryEffects(state).length > 0;

  const gameSpeed = getCurrentTimePieceSpeedupSpeed(state);
  const hasTimePieces = getResourceAmount(state, Resource.TimePiece) > 0;
  const speedupCost = getCurrentTimePieceSpeedupCost(state);
  const timePieceCost = t("{{timepiecePerSec}}:timepiece:/extra sec", {
    timepiecePerSec: formatNumber(speedupCost),
  });

  const showExploration = isExplorationUnlocked(state);

  const theme = useTheme();

  return useMemo(
    () => (
      <ScrollView
        style={[
          styles.scrollContainer,
          { backgroundColor: theme.colors.background },
        ]}
        contentContainerStyle={styles.scrollContentContainer}
      >
        <View style={styles.titleContainer}>
          <Text h1>{t("Magic Research 2")}</Text>
        </View>
        {navigation}
        {hasTimePieces && (
          <CategoryContainer title={t("Speed up time")}>
            <View style={styles.timePieceButtonsContainer}>
              <Text>{t("Set game speed:")}</Text>
              <Button
                onPress={() =>
                  stateRef.current.apply((state) => {
                    state = clearFlag("time_piece_5x_speedup_enabled")(state);
                    state = clearFlag("time_piece_speedup_enabled")(state);
                    return state;
                  })
                }
                compact={true}
                labelStyle={styles.timePieceButtonTitle}
                style={[styles.timePieceButtonContainer]}
              >
                {t("Normal")}
              </Button>
              <Button
                onPress={() =>
                  stateRef.current.apply((state) => {
                    state = clearFlag("time_piece_5x_speedup_enabled")(state);
                    state = setFlag("time_piece_speedup_enabled")(state);
                    return state;
                  })
                }
                compact={true}
                labelStyle={styles.timePieceButtonTitle}
                style={[styles.timePieceButtonContainer]}
              >
                {t("2x")}
              </Button>
              <Button
                onPress={() =>
                  stateRef.current.apply((state) => {
                    state = setFlag("time_piece_5x_speedup_enabled")(state);
                    state = clearFlag("time_piece_speedup_enabled")(state);
                    return state;
                  })
                }
                compact={true}
                labelStyle={styles.timePieceButtonTitle}
                style={[styles.timePieceButtonContainer]}
              >
                {t("5x")}
              </Button>
            </View>
            <View style={styles.timePieceTextRows}>
              <Text>{t("Current speed:")}</Text>
              <Text>{t("{{speed}}x", { speed: gameSpeed })}</Text>
            </View>
            {gameSpeed > 1 && (
              <View style={styles.timePieceTextRows}>
                <View>
                  <Text>{t("Time Piece cost:")}</Text>
                </View>
                <View>
                  <Markdown>{timePieceCost}</Markdown>
                </View>
              </View>
            )}
          </CategoryContainer>
        )}
        <CategoryContainer title={t("Resources")}>
          <View style={styles.statViewer}>
            <ResourceViewer isVisible={props.visible} />
          </View>
        </CategoryContainer>
        <CategoryContainer title={t("Elements")}>
          <View style={styles.statViewer}>
            <ElementViewer isVisible={props.visible} />
          </View>
        </CategoryContainer>
        {showExploration && (
          <CategoryContainer title="Combat">
            <View style={styles.statViewer}>
              <PlayerCombatStatsViewer isVisible={props.visible} />
            </View>
          </CategoryContainer>
        )}
        {hasTemporaryEffects && (
          <CategoryContainer title={t("Buffs")}>
            <TemporaryEffectsViewer isVisible={props.visible} />
          </CategoryContainer>
        )}
      </ScrollView>
    ),
    [
      navigation,
      hasTemporaryEffects,
      hasTimePieces,
      gameSpeed,
      props.visible,
      theme,
    ],
  );
}

