import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { getPlayerActionProgress } from "../../backend/exploration/Exploration";
import { ExplorationStatus } from "../../backend/exploration/ExplorationStatus";
import { getExplorationStatus } from "../../backend/exploration/ExplorationUtils";
import { ProgressBar } from "../utility/ProgressBar";
import { GameIcon } from "../utility/icons/GameIcon";
import { useGameState } from "../utility/useGameState";

export function PlayerActionDisplay() {
  const state = useGameState();
  const inCombat = getExplorationStatus(state) == ExplorationStatus.Combat;
  let progress = 0;
  if (inCombat) {
    progress = getPlayerActionProgress(state);
  }
  return useMemo(
    () => (
      <View style={styles.row}>
        <GameIcon icon="attack" style={styles.icon} />
        <ProgressBar progress={progress} style={{ width: 120 }} />
      </View>
    ),
    [progress],
  );
}

const styles = StyleSheet.create({
  icon: {
    marginRight: 4,
    height: 24,
    width: 24,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
});
