import { valuesOfCached } from "../../../utils/CoreUtils";
import { formatMultiplier } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { ActionEffect } from "../../action/Action";
import { getRankDifference } from "../../spells/ElementMultipliers";
import {
  SpellElement,
  getElementLevel,
  translateElement,
} from "../../spells/Elements";
import { Spell } from "../../spells/Spell";
import {
  TransformationType,
  registerTransformation,
} from "../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../transformation/TransformationTags";
import { getCurrentSynchroBonuses } from "../Synchro";
import { SynchroBonus } from "../SynchroBonus";
import { SynchroBonuses } from "../SynchroBonuses";

class DoubleElementSynchroBonus extends SynchroBonus {
  element: SpellElement;
  constructor(element: SpellElement) {
    super();
    this.element = element;
  }

  getElement1(): SpellElement {
    return this.element;
  }

  getElement2(): SpellElement {
    return this.element;
  }

  protected getBaseBonusEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 0.02,
        valueType: TransformationValueType.Percent,
      },
    };
  }

  getEffect(state: GameState, amount: number): string {
    const magnitude = this.getBonusEffect(state, "magnitude", amount);
    const explanation = this.getBonusEffectExplanations(
      state,
      amount,
      "magnitude",
    )["magnitude"];
    return t(
      "^{{magnitude}}^<{{explanation}}> {{element}} spell power for every {{element}} Rank above the spell's Rank",
      {
        magnitude: formatMultiplier(magnitude),
        explanation,
        element: translateElement(this.element),
      },
    );
  }
}

function loadDoubleElementSynchroBonus(element: SpellElement) {
  const synchroBonus = new DoubleElementSynchroBonus(element);
  SynchroBonuses.register(synchroBonus);

  registerTransformation(
    [
      [
        TransformationTags.Spell,
        TransformationTags.ActionEffect,
        TransformationTags.ActionMagnitude,
        element,
      ],
      [
        TransformationTags.Spell,
        TransformationTags.ActionEffect,
        TransformationTags.TemporaryEffectMagnitude,
        element,
      ],
    ],
    "doubleSynchroBonus" + element,
    t("Synchro Bonus ({{element}}+{{element}})", {
      element: translateElement(element),
    }),
    TransformationType.Multiplier,
    (state, params) => {
      const spell = params.action as Spell;
      const baseLevel = spell.getLevelRequirements()[element];
      if (baseLevel == null) {
        return 1.0;
      }
      const currentLevel = getElementLevel(state, element);
      const rankDifference = getRankDifference(baseLevel, currentLevel);
      if (rankDifference <= 0) {
        return 1.0;
      }
      return Math.pow(
        synchroBonus.getBonusEffect(
          state,
          "magnitude",
          getCurrentSynchroBonuses(state, element, element),
        ),
        rankDifference,
      );
    },
  );

  registerTransformation(
    [[element, TransformationTags.ChannelingEfficiency]],
    "doubleSynchroBonus" + element + "ChannelingMultiplier",
    t("Synchro Bonus ({{element}}+{{element}})", {
      element: translateElement(element),
    }),
    TransformationType.Multiplier,
    (state) => {
      const currentLevel = getElementLevel(state, element);
      const rankDifference = getRankDifference(1, currentLevel);
      return Math.pow(
        synchroBonus.getBonusEffect(
          state,
          "magnitude",
          getCurrentSynchroBonuses(state, element, element),
        ),
        rankDifference,
      );
    },
  );
}

export function loadDoubleElementSynchroBonuses() {
  for (let element of valuesOfCached(SpellElement)) {
    loadDoubleElementSynchroBonus(element);
  }
}
