import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { t } from "../utils/I18n";
import { CorruptionDetails } from "./rituals/CorruptionDetails";
import RitualsView from "./rituals/RitualsView";
import CategoryContainer from "./utility/CategoryContainer";
import Styles from "./utility/Styles";

export function RitualsComponent() {
  return useMemo(
    () => (
      <View style={styles.campusContainer}>
        <CategoryContainer title={t(`Corruption`)}>
          <View style={Styles.mt8}>
            <CorruptionDetails />
          </View>
        </CategoryContainer>
        <View style={Styles.mt8}>
          <CategoryContainer title={t(`Rituals`)}>
            <View style={Styles.mt8}>
              <RitualsView />
            </View>
          </CategoryContainer>
        </View>
      </View>
    ),
    [],
  );
}

const styles = StyleSheet.create({
  campusContainer: {
    height: "100%",
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
});
