import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { monstiumCatalysts } from "../../enhancement/other/MonstiumCatalysts";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { SpellElement } from "../../spells/Elements";
import { registerSpell } from "../../spells/Spells";
import {
  applyTransformationsCached,
  explainTransformationsText,
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import { Buildings, getBuildingAmount } from "../Buildings";

class MonstiumCatalyst extends Building {
  getId(): string {
    return "monstiumCatalyst";
  }

  getName(): string {
    return t("Monstium Catalyst");
  }

  catalystEffectivityTags = [
    TransformationTags.CatalystEffectivity,
    this.getId(),
  ];

  getPerCatalystBonusMultiplier(state: GameState): number {
    return (
      1.0 +
      applyTransformationsCached(this.catalystEffectivityTags, state, 0.12)
    );
  }

  catalystEffectivityParams = {
    valueType: TransformationValueType.Percent,
  };

  getPerCatalystBonusMultiplierExplanation(state: GameState): string {
    return explainTransformationsText(
      this.catalystEffectivityTags,
      state,
      0.12,
      this.catalystEffectivityParams,
    );
  }

  getDisplayDescription(state: GameState): string {
    return t("A catalyst that drastically improves Monstium gain.");
  }

  getDisplayEffect(state: GameState): string {
    const effectPct = (this.getPerCatalystBonusMultiplier(state) - 1.0) * 100;
    return t(
      "^+{{effectPct}}%^<{{explanation}}>:monstium: obtained from enemies",
      {
        effectPct: formatNumber(effectPct),
        explanation: this.getPerCatalystBonusMultiplierExplanation(state),
      },
    );
  }

  getBaseLandRequired(): number {
    return 10;
  }

  canTurnOff(): boolean {
    return false;
  }
}

export const monstiumCatalyst = new MonstiumCatalyst();

class BuildMonstiumCatalyst extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.CATALYSTS] };
  }

  getBuilding(): MonstiumCatalyst {
    return monstiumCatalyst;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      PoisonEssence: 4000,
      FireEssence: 2000,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      PoisonEssence: 1.8,
      FireEssence: 1.8,
    };
  }

  isVisible(state: GameState): boolean {
    return monstiumCatalysts.isPurchased(state) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Poison;
  }
}

export const buildMonstiumCatalyst = new BuildMonstiumCatalyst();

export function loadMonstiumCatalysts() {
  Buildings.register(monstiumCatalyst);
  registerSpell(buildMonstiumCatalyst);

  registerTransformation(
    [[TransformationTags.Loot, Resource.Monstium]],
    "monstiumCatalyst",
    t("Monstium Catalyst"),
    TransformationType.Multiplier,
    (state, params) => {
      const building = monstiumCatalyst;
      return Math.pow(
        building.getPerCatalystBonusMultiplier(state),
        getBuildingAmount(state, building),
      );
    },
  );
}
