import { valuesOf } from "../../utils/CoreUtils";
import { cacheIndefinitely, clearCacheKey } from "../Caching";
import { GameState } from "../GameState";
import { LevelUpListeners } from "../events/progression/LevelUp";
import { addEnemyDeathListener } from "../exploration/Exploration";
import { Enemies } from "../exploration/enemies/Enemies";
import {
  BestiaryState,
  Enemy,
  KILLS_FOR_BESTIARY_LOOT,
  getCumulativeEnemyKills,
} from "../exploration/enemies/Enemy";
import { AddToInventoryForFirstTimeListeners } from "./Inventory";
import { Item } from "./Item";
import { Items } from "./Items";
import {
  TransmutationSpell,
  getAllTransmutationSpellsEverCastable,
  getTransmutationSpellForItem,
} from "./transmute/TransmutationSpell";

export type ItemCompendiumLootInfo = {
  enemy: Enemy;
  lootChance: number | undefined;
  amount: number | undefined;
};

export type ItemCompendiumInfo = {
  item: Item;
  transmuteSpell: TransmutationSpell | undefined;
  knownLootFromEnemies: ItemCompendiumLootInfo[];
};

export function getItemCompendiumInfoForItem(
  state: GameState,
  itemId: string,
): ItemCompendiumInfo {
  const item = Items.getById(itemId);
  const info: ItemCompendiumInfo = {
    item,
    transmuteSpell: undefined,
    knownLootFromEnemies: [],
  };
  const transmutationSpell = getTransmutationSpellForItem(item);
  const transmutationSpellsEverCastable =
    getAllTransmutationSpellsEverCastable(state);
  if (
    transmutationSpell != null &&
    transmutationSpellsEverCastable.includes(transmutationSpell)
  ) {
    info.transmuteSpell = transmutationSpell;
  }

  // Now, check all the enemies that can drop it
  const enemyInfo = {};
  Enemies.getAll().forEach((enemy) => {
    const loot = enemy.getItemsAwardedBase(state);
    const foundLoot = loot.find((enemyLoot) => enemyLoot.itemId == itemId);
    if (foundLoot != null) {
      if (enemy.getBestiaryState(state) == BestiaryState.Loot) {
        enemyInfo[enemy.getId()] = {
          enemy: enemy,
          lootChance: foundLoot.chance,
          amount: foundLoot.amount,
        };
      } else {
        // We only return it if we have seen a drop
        if (state.world.knownEnemiesThatDrop[itemId]?.[enemy.getId()]) {
          enemyInfo[enemy.getId()] = {
            enemy: enemy,
            lootChance: undefined,
            amount: foundLoot.amount,
          };
        }
      }
    }
  });
  info.knownLootFromEnemies = valuesOf(enemyInfo);
  return info;
}

export function getAllKnownItemCompendiumInfos(
  state: GameState,
): ItemCompendiumInfo[] {
  return cacheIndefinitely(
    state,
    (_state) => "allKnownItemCompendiumInfos",
    (state) =>
      Items.getAll()
        .map((item) => getItemCompendiumInfoForItem(state, item.getId()))
        .filter(
          (info) =>
            info.transmuteSpell ||
            info.knownLootFromEnemies.length > 0 ||
            state.world.itemsEverAddedToInventory[info.item.getId()],
        ),
  );
}

function clearItemCompendiumInfoCache(state: GameState): GameState {
  return clearCacheKey(state, (_state) => "allKnownItemCompendiumInfos");
}

export function loadCompendium() {
  LevelUpListeners.register(
    clearItemCompendiumInfoCache,
    "compendiumCacheClearerLevelUp",
  );
  addEnemyDeathListener("compendiumCacheClearerEnemyDeath", (state, enemy) => {
    if (enemy != null) {
      const totalCounter = getCumulativeEnemyKills(state, enemy.getId());
      if (
        (totalCounter == 1 && enemy.isBoss()) ||
        (totalCounter == KILLS_FOR_BESTIARY_LOOT && !enemy.isBoss())
      ) {
        return clearItemCompendiumInfoCache(state);
      }
    }
    return state;
  });
  AddToInventoryForFirstTimeListeners.register(
    clearItemCompendiumInfoCache,
    "compendiumCacheClearerAddToInventoryFirstTime",
  );
}
