import { GameState } from "./GameState";

export function incrementAccumulatorVariable(
  state: GameState,
  variableName: string,
  amount?: number,
): GameState {
  const currentValue = (state.run.variables?.[variableName] as number) ?? 0;
  state.run.variables[variableName] = currentValue + (amount ?? 0);
  const worldValue = (state.world.variables?.[variableName] as number) ?? 0;
  state.world.variables[variableName] = worldValue + (amount ?? 0);
  return state;
}

export function getAccumulatorVariable(
  state: GameState,
  variableName: string,
): number {
  return (state.run.variables?.[variableName] as number) ?? 0;
}

export function getWorldAccumulatorVariable(
  state: GameState,
  variableName: string,
): number {
  return (state.world.variables?.[variableName] as number) ?? 0;
}
