import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { AttackTarget } from "../../exploration/AttackTarget";
import { GameState } from "../../GameState";
import { Spell } from "../../spells/Spell";
import {
  getTemporaryEffectEndTime,
  TemporaryEffects,
} from "../../temporaryeffects/TemporaryEffects";
import { getSecondsPlayed } from "../../timetick/TotalTimePlayed";
import {
  AutocastCondition,
  AutocastConditionParameter,
} from "../AutocastCondition";
import { NumericAutocastConditionParameterType } from "./parameters/NumericAutocastConditionParameterType";
import { TemporaryEffectAutocastConditionParameterType } from "./parameters/TemporaryEffectAutocastConditionParameterType";

class TemporaryEffectLowTimeRemainingAutocastCondition extends AutocastCondition {
  getId(): string {
    return "IfBuffNotPresentOrTimeRemainingLessThanPlayer";
  }

  getDisplayName(): string {
    return t(
      "If buff / debuff is not present on player or its time left is less than",
    );
  }

  getParameters(): AutocastConditionParameter[] {
    return [
      {
        parameterType: new TemporaryEffectAutocastConditionParameterType(),
        name: t("Buff / debuff"),
      },
      {
        parameterType: new NumericAutocastConditionParameterType(0, undefined),
        name: t("Time remaining (sec)"),
      },
    ];
  }

  getDisplayExplanation(state: GameState, values: string[]): string {
    return t(
      "If player's buff / debuff *{{buff}}* is over or has less than {{amount}} sec left",
      {
        buff: TemporaryEffects.getById(values[0]).getDisplayName(),
        amount: formatNumber(Number.parseFloat(values[1])),
      },
    );
  }

  isConditionMet(
    state: GameState,
    spell: Spell | undefined,
    values: string[],
  ): boolean {
    const temporaryEffectId = values[0];
    const timeLeftSecs = Number.parseFloat(values[1]);
    const actualTimeLeft =
      getTemporaryEffectEndTime(state, temporaryEffectId, AttackTarget.Player) -
      getSecondsPlayed(state);
    return actualTimeLeft < timeLeftSecs;
  }
}

export default new TemporaryEffectLowTimeRemainingAutocastCondition();
