import React from "react";
import { AttackTarget } from "../../backend/exploration/AttackTarget";
import {
  getCurrentCompanionClass,
  getCurrentCompanionData,
} from "../../backend/familiars/Familiar";
import { useGameState } from "../utility/useGameState";
import { HPBar } from "./HPBar";

export function FamiliarHPBar() {
  const state = useGameState();
  const currentHP = getCurrentCompanionData(state)?.status?.currentHp || 0;
  let maxHP =
    getCurrentCompanionClass(state)?.getStatsForCompanion(state)?.maxHP || 1;
  return (
    <HPBar
      empty={false}
      current={currentHP}
      max={maxHP}
      actionTarget={AttackTarget.Familiar}
    />
  );
}
