import { useMemo } from "react";
import { View } from "react-native";
import { getConcatenatedRequirements } from "../../backend/events/progression/LevelUp";
import { ItemCompendiumInfo } from "../../backend/items/ItemCompendium";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ItemInfo } from "../inventory/ItemInfo";
import { Markdown } from "../utility/Markdown";

export function ItemCompendiumDetails(props: { info: ItemCompendiumInfo }) {
  const info = props.info;
  const item = info.item;
  const params = item.getDefaultParams();
  const spell = info.transmuteSpell;

  const howToObtainStrings = [];
  if (spell != null) {
    const concatenatedRequirements = getConcatenatedRequirements(spell);
    howToObtainStrings.push(
      t("Transmutation ({{requirements}})", {
        requirements: concatenatedRequirements,
      }),
    );
  }
  info.knownLootFromEnemies.forEach((lootInfo) =>
    howToObtainStrings.push(
      lootInfo.lootChance != null
        ? t("{{enemyName}} ({{chance}}% to get {{amount}})", {
            enemyName: lootInfo.enemy.getName(),
            amount: lootInfo.amount,
            chance: formatNumber(lootInfo.lootChance * 100, {
              showDecimals: true,
            }),
          })
        : t("{{enemyName}} (???)", { enemyName: lootInfo.enemy.getName() }),
    ),
  );

  const howToObtainText = t("How to obtain: *{{howToObtain}}*", {
    howToObtain:
      howToObtainStrings.length > 0
        ? howToObtainStrings.join(t(", "))
        : t("Unknown"),
  });

  const howToObtainComponent = useMemo(
    () => (
      <View>
        <Markdown>{howToObtainText}</Markdown>
      </View>
    ),
    [howToObtainText],
  );

  const itemStack = useMemo(
    () => ({
      itemOccurrence: { itemId: item.getId(), params },
      amount: 1,
    }),
    [item.getId()],
  );

  return useMemo(
    () => (
      <View>
        <ItemInfo itemStack={itemStack} showTile={true} />
        {howToObtainComponent}
      </View>
    ),
    [itemStack, howToObtainComponent],
  );
}
