import { keysOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../action/ActionAreas";
import { GameState } from "../GameState";
import { Resource } from "../Resources";
import {
  createNewVat,
  isSpellcraftUnlocked,
} from "../spellcraft/SpellcraftUtils";
import { SpellElement } from "../spells/Elements";
import { Building, BuildingSpell } from "./Building";
import { BuildingAmountListeners, getBuildingAmount } from "./Buildings";

class SpellcraftingVat extends Building {
  getId(): string {
    return "spellcraftingVat";
  }

  getName(): string {
    return t("Spellcrafting Vat");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "An oversized tube meant to hold large amounts of items, so that their power may be routed into a spell.",
    );
  }

  getDisplayEffect(state: GameState): string {
    return t(
      "+1 Spellcraft slot. Note: Cannot be demolished - destroy the vat instead from the Spellcraft screen",
    );
  }

  getBaseLandRequired(): number {
    return 15;
  }

  canTurnOff(): boolean {
    return false;
  }
}

export const spellcraftingVat = new SpellcraftingVat();

class BuildSpellcraftingVat extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.OTHER] };
  }

  getBuilding(): Building {
    return spellcraftingVat;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Coins: 10000,
      AirEssence: 12000,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      Coins: 4.0,
      AirEssence: 4.0,
    };
  }

  isVisible(state: GameState): boolean {
    return isSpellcraftUnlocked(state) && super.isVisible(state);
  }

  shouldHideDemolish(): boolean {
    return true;
  }

  canDemolish(state: GameState, amount: number): boolean {
    return false;
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }
}

export const buildSpellcraftingVat = new BuildSpellcraftingVat();

function vatEqualizer(state: GameState) {
  const buildingAmt = getBuildingAmount(state, spellcraftingVat);
  const vatAmt = keysOf(state.run.spellcrafts).length;
  if (vatAmt < buildingAmt) {
    const buildingAmt = getBuildingAmount(state, spellcraftingVat);
    let vatAmt = keysOf(state.run.spellcrafts).length;
    while (vatAmt < buildingAmt) {
      state = createNewVat(state);
      vatAmt++;
    }
  }
  return state;
}

BuildingAmountListeners.register(vatEqualizer, "spellcraftingVats");
