import memoize from "sonic-memoize";
import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { Resource, clearResourceCapCache, grantResource } from "../Resources";
import { Action, DoActionArgs } from "../action/Action";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../action/ActionAreas";
import { getVisibleActionsForAreaBySubcategory } from "../action/Actions";
import { BoostingMachine } from "../events/storyline/desert/BoostingMachineStoryline";
import { AttackTarget } from "../exploration/AttackTarget";
import { clearCombatStatCache } from "../exploration/CombatStats";
import { SpellElement, getUnlockedElements } from "../spells/Elements";
import { RESOURCE_FOR_ELEMENT } from "../spells/channeling/ChannelingResources";
import { clearCalculatedIncomeCache } from "../timetick/IncomeOverTime";
import { applyTransformations } from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";

abstract class BoostActionBase extends Action {
  constructor() {
    super();
  }

  isVisible(state: GameState): boolean {
    return getUnlockedElements(state).includes(this.getElement());
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { BOOST: [ActionSubcategories.BOOST] };
  }

  getTags(): string[] {
    return super.getTags().concat([TransformationTags.Boost]);
  }

  abstract getName(): string;

  getDisplayName(state: GameState): string {
    const bought = getBoughtBoostsAmount(state, this.getElement());
    return this.getName() + (bought > 0 ? ` (${bought})` : "");
  }

  abstract getElement(): SpellElement;

  _costScaleMonstium = memoize(() => [
    ...this.getTags(),
    Resource.Monstium,
    TransformationTags.CostScale,
  ]);
  _costScaleEssence = memoize((essence) => [
    ...this.getTags(),
    essence,
    TransformationTags.CostScale,
  ]);

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    const element = this.getElement();
    const essence = RESOURCE_FOR_ELEMENT[element];
    const fractionAddedPerUpgrade = applyTransformations(
      this._costScaleMonstium(),
      state,
      0.2,
    );
    const essenceFractionAddedPerUpgrade = applyTransformations(
      this._costScaleEssence(essence),
      state,
      0.4,
    );
    const resourcesObject = {
      Monstium:
        Math.pow(1.0 + fractionAddedPerUpgrade, getTotalBoughtBoosts(state)) *
        100,
    };
    resourcesObject[essence] =
      Math.pow(
        1.0 + essenceFractionAddedPerUpgrade,
        getBoughtBoostsAmount(state, element),
      ) * 1000;
    return {
      resources: resourcesObject,
      items: {},
    };
  }

  abstract getTotalBoostDescription(state: GameState): string;

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }
    const cost = this.getCost(state).resources;
    state.run.boostsBought[this.getElement()]++;
    state = grantResource(Resource.Monstium, -(cost.Monstium ?? 0))(state);
    state = grantResource(
      RESOURCE_FOR_ELEMENT[this.getElement()],
      -(cost[RESOURCE_FOR_ELEMENT[this.getElement()]] ?? 0),
    )(state);
    state = clearCombatStatCache(state, AttackTarget.Player);
    state = clearCalculatedIncomeCache(state);
    state = clearResourceCapCache(state);
    return state;
  }

  canAutoPurchaseMax(): boolean {
    return true;
  }
}

export function isBoostingUnlocked(state: GameState): boolean {
  return BoostingMachine.isBonusActive(state);
}

export function getBoughtBoostsAmount(
  state: GameState,
  element: SpellElement,
): number {
  return state.run.boostsBought[element];
}

export function hasBoosts(state: GameState) {
  return getTotalBoughtBoosts(state) > 0;
}

export function getBoostsBonusText(state: GameState): string {
  const visibleBoosts = (getVisibleActionsForAreaBySubcategory(
    state,
    ActionArea.BOOST,
  )?.[ActionSubcategories.BOOST] ?? []) as BoostActionBase[];
  return visibleBoosts
    .map((visibleBoost) => {
      if (getBoughtBoostsAmount(state, visibleBoost.getElement()) <= 0) {
        return null;
      }
      return visibleBoost.getTotalBoostDescription(state);
    })
    .filter((value) => value != null)
    .join(t(", "));
}

export function getTotalBoughtBoosts(state: GameState): number {
  return valuesOf(SpellElement)
    .map((element) => getBoughtBoostsAmount(state, element))
    .reduce((prev, current) => prev + current, 0);
}

export default BoostActionBase;
