import { valuesOfCached } from "../../utils/CoreUtils";
import { GameState } from "../GameState";
import { ActionArea } from "../action/ActionAreas";

export function clearVisibleSpellsCache(state: GameState): GameState {
  for (let actionArea of [...valuesOfCached(ActionArea), "undefined"]) {
    const key = "spellByElement:" + actionArea;
    if (state.temp.cache.get(key) != null) {
      state.temp.cache.delete(key);
    }
  }
  return state;
}
