import { t } from "../../../../../utils/I18n";
import { GameState } from "../../../../GameState";
import { Resource } from "../../../../Resources";
import { SpellElement } from "../../../../spells/Elements";
import { EquipmentSlot } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class ReinforcedLeatherArmor extends EquippableItem {
  getId(): string {
    return "reinforcedLeatherArmor";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Body;
  }
  getPicture() {
    return require("./reinforcedLeatherArmor.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Reinforced Leather Armor");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t("A sturdy light armor, with good straightforward protection.");
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 3500;
  }

  getDefenseBonusBase(params: ItemParams): number {
    return 65;
  }
}

export const reinforcedLeatherArmor = new ReinforcedLeatherArmor();

class TransmuteReinforcedLeatherArmor extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return reinforcedLeatherArmor;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Fire: 21,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 600,
        EarthEssence: 18000,
      },
      items: {
        elementalShardFire: 50,
        elementalShardEarth: 5,
        thickHide: 10,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Fire;
  }
}

export const transmuteReinforcedLeatherArmor =
  new TransmuteReinforcedLeatherArmor();
