import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { CombatActionResultListeners } from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import { addGameStateListener } from "../GameStateListeners";
import { SpellElement } from "../spells/Elements";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  clearTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectCombatStatCacheClearer,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";
import { Wet } from "./Wet";

class Chilled extends TemporaryEffect {
  getId(): string {
    return "chilled";
  }
  getDisplayName(): string {
    return t("Chilled");
  }
  isBeneficial(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "+25%:attackDelay:; next Fire attack received is halved in damage",
    );
  }
}

const chilled = new Chilled();

export { chilled as Chilled };

export function loadChilled() {
  TemporaryEffects.register(chilled);

  registerTransformation(
    [[TransformationTags.AttackDamageReceived]],
    "chilled",
    chilled.getDisplayName(),
    TransformationType.Multiplier,
    (state, params) => {
      return hasTemporaryEffect(state, chilled.getId(), params.target) &&
        (params.tags ?? []).includes(SpellElement.Fire)
        ? 0.5
        : 1.0;
    },
  );

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[CombatStat.AttackDelay, target]],
      "chilled" + target,
      chilled.getDisplayName(),
      TransformationType.Multiplier,
      (state, params) => {
        return hasTemporaryEffect(state, chilled.getId(), target) ? 1.25 : 1.0;
      },
    );
  }

  registerTemporaryEffectCombatStatCacheClearer(
    chilled.getId(),
    CombatStat.AttackDelay,
  );

  CombatActionResultListeners.register((state, result) => {
    if (
      result.tags.includes(SpellElement.Fire) &&
      result.isHit &&
      hasTemporaryEffect(state, chilled.getId(), result.target)
    ) {
      state = clearTemporaryEffect(chilled.getId(), result.target)(state);
    }
    return state;
  });

  addGameStateListener("wetClearerIfChilled", (state) => {
    for (let target of valuesOf(AttackTarget)) {
      if (
        hasTemporaryEffect(state, chilled.getId(), target) &&
        hasTemporaryEffect(state, Wet.getId(), target)
      ) {
        return clearTemporaryEffect(Wet.getId(), target);
      }
    }
  });
}
