import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasEverHadFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource, getResourceAmount, grantResource } from "../../Resources";
import { triggerRandomEvent } from "../../events/autorandom/RandomEventTrigger";
import { recordTimePieceConsumption } from "../../timetick/TimePieces";
import { Action, ActionEffect, DoActionArgs } from "../Action";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../ActionAreas";

class UseTimePiecesEvent extends Action {
  getId(): string {
    return "useTimePiecesEvent";
  }

  getDisplayName(state: GameState): string {
    return t("Use Time Pieces (Event)");
  }

  isVisible(state: GameState): boolean {
    return (
      getResourceAmount(state, Resource.TimePiece) > 0 &&
      hasEverHadFlag(state, Flag.ExplorationUnlocked)
    );
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return {
      HOME: [ActionSubcategories.TIME_PIECES],
    };
  }

  getDisplayDescription(): string {
    return "Use your time pieces to immediately trigger a random occurrence.";
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {};
  }

  getDisplayEffect(state: GameState): string {
    return t(`Instantly trigger the next random event`);
  }

  canAfford(state: GameState): boolean {
    return (
      super.canAfford(state) &&
      getResourceAmount(state, Resource.TimePiece) >= 1800
    );
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        TimePiece: 1800,
      },
      items: {},
    };
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }

    state = grantResource(
      Resource.TimePiece,
      -(this.getCost(state).resources[Resource.TimePiece] ?? 0),
    )(state);

    state = recordTimePieceConsumption(
      state,
      this.getCost(state).resources[Resource.TimePiece] ?? 0,
    );

    return triggerRandomEvent(state);
  }
}

export default new UseTimePiecesEvent();
