import { getDocumentAsync } from "expo-document-picker";
import * as FileSystem from "expo-file-system";
import React, { useCallback, useMemo, useRef, useState } from "react";
import { I18nManager, Platform, StyleSheet, View } from "react-native";
import { Card, TextInput } from "react-native-paper";
import AudioManager from "../audio/AudioManager";
import { cleanup } from "../backend/Cleanup";
import { clearFlag, hasFlag, setFlag } from "../backend/Flags";
import { Resource, grantResource } from "../backend/Resources";
import {
  loadBase64Data,
  makeCompatible,
  saveDataUnsafe,
} from "../backend/SaveData";
import { getAllGameEvents, triggerEvent } from "../backend/events/Events";
import { debugRandomEvents } from "../backend/events/autorandom/RandomEventTrigger";
import { AttackTarget } from "../backend/exploration/AttackTarget";
import {
  CombatStat,
  clearCombatStatCache,
} from "../backend/exploration/CombatStats";
import { SpellElement, grantElementExp } from "../backend/spells/Elements";
import { Storylines } from "../backend/storylines/Storylines";
import { getSecondsPlayed } from "../backend/timetick/TotalTimePlayed";
import {
  TransformationType,
  explainTransformations,
  registerTransformation,
} from "../backend/transformation/Transformation";
import { keysOf } from "../utils/CoreUtils";
import { saveToFile } from "../utils/FilesystemSave";
import { formatNumber } from "../utils/FormattingUtils";
import { Storage } from "../utils/Storage";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import { Picker } from "./utility/Picker";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

export default function Debug() {
  const events = getAllGameEvents();
  const storylines = Storylines.getAll();
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const [selectedEvent, setSelectedEvent] = useState("");

  const picker = useMemo(
    () => (
      <Picker
        onValueChange={(value) => setSelectedEvent(value)}
        selectedValue={selectedEvent}
        data={Object.values(events).map((event, idx) => ({
          label: event.getId(),
          value: event.getId(),
          key: event.getId(),
        }))}
      />
    ),
    [setSelectedEvent, selectedEvent, keysOf(events).join()],
  );
  const eventButtons = useMemo(
    () => (
      <CategoryContainer title="Trigger events">
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              state.run.nextRandomEvent = 0;
              return state;
            })
          }
        >
          Trigger random event
        </Button>
        {picker}
        <Button
          onPress={() =>
            stateRef.current.apply(triggerEvent(events[selectedEvent]))
          }
        >
          Trigger specific event
        </Button>
      </CategoryContainer>
    ),
    [JSON.stringify(Object.keys(events)), selectedEvent],
  );

  const [selectedStoryline, setSelectedStoryline] = useState("");

  const pickerStoryline = useMemo(
    () => (
      <Picker
        onValueChange={(value) => setSelectedStoryline(value)}
        selectedValue={selectedStoryline}
        data={storylines.map((storyline, idx) => ({
          label: storyline.getId(),
          value: storyline.getId(),
          key: storyline.getId(),
        }))}
      />
    ),
    [setSelectedStoryline, selectedStoryline],
  );
  const storylineButtons = useMemo(
    () => (
      <CategoryContainer title="Trigger Storylines">
        {pickerStoryline}
        <Button
          onPress={() =>
            stateRef.current.apply((state) =>
              Storylines.getById(selectedStoryline).complete(state),
            )
          }
        >
          Complete Storyline
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) =>
              Storylines.getById(selectedStoryline).activateBonus(state),
            )
          }
        >
          Activate Storyline bonus
        </Button>
      </CategoryContainer>
    ),
    [selectedStoryline],
  );

  const [newPlayerContextState, setNewPlayerContextState] = useState(
    JSON.stringify(state, null, "  "),
  );
  const [fixingPlayerContextState, setFixingPlayerContextState] = useState("");
  const overridePlayerContext = useCallback(async () => {
    const deserializedJSON = JSON.parse(newPlayerContextState);
    stateRef.current.overwrite((_oldState) => {
      return makeCompatible(deserializedJSON);
    });
    await saveDataUnsafe(deserializedJSON);
  }, [newPlayerContextState]);

  const [transformDebugInput, setTransformDebugInput] = useState("");
  const [transformDebugOutput, setTransformDebugOutput] = useState("");

  const calculate = useCallback(() => {
    const tags = transformDebugInput.split(",");

    const output = explainTransformations(tags, stateRef.current, 1.0);
    const debugOutput = Object.keys(output)
      .map((key) => {
        const eachType = output[key as TransformationType].map(
          ({ id, value }) =>
            `${id}: ${
              value != null
                ? formatNumber(value, { showDecimals: true })
                : "undefined"
            }`,
        );
        return key + ": " + eachType.join(", ");
      })
      .join("\n");
    setTransformDebugOutput(debugOutput);
  }, [transformDebugInput, setTransformDebugOutput]);

  const playSoundCallback = useCallback(
    () => AudioManager.play("menuClick"),
    [],
  );

  return (
    <View style={styles.container}>
      <CategoryContainer title="Other actions">
        <Button
          onPress={async () => {
            const result = await getDocumentAsync();
            if (result.type == "success") {
              const resultContents = await FileSystem.readAsStringAsync(
                result.uri,
              );
              const unserialized = await loadBase64Data(resultContents);
              console.warn(unserialized);
            }
            console.warn(result);
          }}
        >
          Open Document Picker
        </Button>
        <Button enableAutoRepress={true} onPress={playSoundCallback}>
          Play Sounds
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              for (let resource in stateRef.current.run.resources) {
                if (resource != Resource.TimePiece) {
                  state = grantResource(resource as Resource, 1e30)(state);
                }
              }
              // state = grantResource(Resource.TimePieces, 9999999)(state);
              return state;
            })
          }
        >
          Max all storages
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              state.global.debugShowUpdatesPerSec =
                !state.global.debugShowUpdatesPerSec;
              return state;
            })
          }
        >
          Toggle UPS overlay
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              if (hasFlag(state, "machinegun_mode")) {
                state = clearFlag("machinegun_mode")(state);
              } else {
                state = setFlag("machinegun_mode")(state);
              }
              state = clearCombatStatCache(state, AttackTarget.Player);
              return state;
            })
          }
        >
          Toggle machinegun mode
        </Button>
        <Button onPress={() => I18nManager.forceRTL(true)}>Force RTL</Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              state = grantElementExp(SpellElement.Fire, 10000)(state);
              state = grantElementExp(SpellElement.Earth, 10000)(state);
              state = grantElementExp(SpellElement.Water, 10000)(state);
              state = grantElementExp(SpellElement.Air, 10000)(state);
              return state;
            })
          }
        >
          Grant 10k element exp
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              state.run.warpTimeSecs = 60 * 10;
              return state;
            })
          }
        >
          Warp 10 minutes
        </Button>
        <Button
          onPress={() => {
            stateRef.current.apply((state) => {
              state.run.skipAheadTime = 60 * 10;
              return state;
            });
          }}
        >
          Artificially pass 10 minutes
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              if (hasFlag(state, "debug_2x_speed")) {
                state = clearFlag("debug_2x_speed")(state);
              } else {
                state = setFlag("debug_2x_speed")(state);
              }
              return state;
            })
          }
        >
          Toggle 2x speed
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              if (hasFlag(state, "debug_0x_speed")) {
                state = clearFlag("debug_0x_speed")(state);
              } else {
                state = setFlag("debug_0x_speed")(state);
              }
              return state;
            })
          }
        >
          Toggle 0x speed
        </Button>
        <Button
          onPress={() =>
            stateRef.current.apply((state) => {
              if (hasFlag(state, "debug_invincibility")) {
                state = clearFlag("debug_invincibility")(state);
              } else {
                state = setFlag("debug_invincibility")(state);
              }
              state = clearCombatStatCache(state, AttackTarget.Player);
              return state;
            })
          }
        >
          Toggle invincibility
        </Button>
        <Button
          onPress={() => {
            state.apply(cleanup);
          }}
          key="cleanupplayercontext"
        >
          Run cleanup
        </Button>
        <Button
          onPress={() => {
            Storage.set(
              "uploadedTranslationsJSON",
              `{"Advanced Furniture Preservation ({{furniture}})": "YE{{furniture}}SYESYESSSS", "**Full Version**": "NONONNOO"}`,
            );
          }}
          key="settranslations"
        >
          Set mock translations
        </Button>
      </CategoryContainer>
      <Card>
        <Card.Title title="Multiplier Debugging"></Card.Title>
        <Text>Separate tags by commas</Text>
        <TextInput
          onChangeText={setTransformDebugInput}
          value={transformDebugInput}
          onSubmitEditing={calculate}
        />
        <Button onPress={calculate}>Calculate Multipliers</Button>
        <Text>{transformDebugOutput}</Text>
      </Card>
      <Card>
        <Card.Title title="Random Event Debugging"></Card.Title>
        <Text>
          Next event in{" "}
          {formatNumber(
            state.run.nextRandomEvent -
              state.run.extraEventSecs -
              getSecondsPlayed(state),
          )}{" "}
          sec
        </Text>
        <Text>{JSON.stringify(debugRandomEvents(state), null, "\t")}</Text>
      </Card>
      <Card>
        <Card.Title title="Game State Editing"></Card.Title>
        <TextInput
          multiline={true}
          onChangeText={setNewPlayerContextState}
          value={newPlayerContextState}
          contentStyle={{ height: 400 }}
          mode="outlined"
          style={{
            height: 400,
            fontFamily: Platform.select({
              android: "monospace",
              default: "Courier New",
            }),
          }}
        />
        <Button onPress={overridePlayerContext} key="setPlayerContext">
          Set State
        </Button>
      </Card>

      <Card>
        <Card.Title title="Game State Fixing"></Card.Title>
        <TextInput
          multiline={true}
          onChangeText={setFixingPlayerContextState}
          value={fixingPlayerContextState}
          mode="outlined"
          style={{
            height: 400,
            fontFamily: Platform.select({
              android: "monospace",
              default: "Courier New",
            }),
          }}
        />
        <Button
          onPress={async () => {
            const unserialized = await loadBase64Data(fixingPlayerContextState);
            setFixingPlayerContextState(
              JSON.stringify(unserialized, null, "  "),
            );
          }}
          key="fixPlayerContext"
        >
          Load
        </Button>
        <Button
          onPress={() => {
            saveToFile(JSON.parse(fixingPlayerContextState));
          }}
          key="getAsFile"
        >
          Save to file
        </Button>
      </Card>

      <Card>
        <Card.Title title="Trigger Events"></Card.Title>
        {eventButtons}
      </Card>
      <Card>
        <Card.Title title="Trigger Storylines"></Card.Title>
        {storylineButtons}
      </Card>
    </View>
  );
}

registerTransformation(
  [[AttackTarget.Player, CombatStat.AttackDelay]],
  "machinegun_delay",
  "Machinegun Mode",
  TransformationType.Override,
  (state) => (hasFlag(state, "machinegun_mode") ? 0.01 : undefined),
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Attack]],
  "machinegun_attack",
  "Machinegun Mode",
  TransformationType.Override,
  (state) => (hasFlag(state, "machinegun_mode") ? 1 : undefined),
);

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
});
