import { Enemies } from "./Enemies";
import EtherealEel from "./sewers/EtherealEel";
import EvolvedSlime from "./sewers/EvolvedSlime";
import Goblin from "./sewers/Goblin";
import GoblinChief from "./sewers/GoblinChief";
import GoblinDoctor from "./sewers/GoblinDoctor";
import GoblinSharpshooter from "./sewers/GoblinSharpshooter";
import { loadManEatingPlant } from "./sewers/ManEatingPlant";
import MutatedFish from "./sewers/MutatedFish";
import PoisonousToad from "./sewers/PoisonousToad";
import SewerFish from "./sewers/SewerFish";
import SewerFly from "./sewers/SewerFly";
import Sicklefish from "./sewers/Sicklefish";
import SludgeMass from "./sewers/SludgeMass";
import VenomousSpider from "./sewers/VenomousSpider";
import AnimatedSnowman from "./training/AnimatedSnowman";
import BlazingTurtle from "./training/BlazingTurtle";
import BreezyRabbit from "./training/BreezyRabbit";
import FireBat from "./training/FireBat";
import FlyingRock from "./training/FlyingRock";
import Goat from "./training/Goat";
import HeadInstructor from "./training/HeadInstructor";
import KnightInShiningArmor from "./training/KnightInShiningArmor";
import RockGolem from "./training/RockGolem";
import Slime from "./training/Slime";
import FamiliarOfWind from "./training/Windglider";

export function loadEnemiesDemo() {
  // Training Towers
  Enemies.register(Slime);
  Enemies.register(FlyingRock);
  Enemies.register(RockGolem);
  Enemies.register(BreezyRabbit);
  Enemies.register(FamiliarOfWind);
  Enemies.register(FireBat);
  Enemies.register(BlazingTurtle);
  Enemies.register(Goat);
  Enemies.register(AnimatedSnowman);
  Enemies.register(KnightInShiningArmor);
  Enemies.register(HeadInstructor);

  // Sewers
  Enemies.register(EvolvedSlime);
  Enemies.register(PoisonousToad);
  Enemies.register(SewerFish);
  Enemies.register(Goblin);
  Enemies.register(MutatedFish);
  Enemies.register(Sicklefish);
  Enemies.register(SewerFly);
  Enemies.register(GoblinSharpshooter);
  Enemies.register(SludgeMass);
  Enemies.register(GoblinChief);
  Enemies.register(VenomousSpider);
  Enemies.register(GoblinDoctor);
  Enemies.register(EtherealEel);
  loadManEatingPlant();
}
