import React, { useMemo } from "react";
import { View } from "react-native";
import { getVisibleRituals } from "../../backend/rituals/Rituals";
import Divider from "../utility/Divider";
import { useGameState } from "../utility/useGameState";
import { RitualView } from "./RitualView";

export default function RitualsView(props: {}) {
  const state = useGameState();
  const rituals = getVisibleRituals(state);
  const ritualsIdString = rituals.map((ritual) => ritual.getId()).join("");

  const ritualContainers = useMemo(
    () =>
      rituals.map((ritual) => {
        return <RitualView key={ritual.getId()} ritual={ritual} />;
      }),
    [ritualsIdString],
  );

  return useMemo(() => {
    if (rituals.length === 0) {
      return null;
    }
    return (
      <View>
        <View key={rituals[0].getId()}>{ritualContainers[0]}</View>
        {ritualContainers.slice(1).map((ritualContainer, idx) => {
          return (
            <View key={rituals[idx].getId()}>
              <Divider />
              {ritualContainer}
            </View>
          );
        })}
      </View>
    );
  }, [ritualsIdString, ritualContainers]);
}
