import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { clearVisibleSpellsCache } from "../../spells/VisibleSpellsCache";
import { Enhancement } from "../Enhancement";
import { EnhancementPurchaseListeners } from "../EnhancementPurchaseListeners";

class ManaSteamers extends Enhancement {
  getId(): string {
    return "manaSteamers";
  }
  getBaseName(): string {
    return t("Mana Steamers");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_OTHER;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 250,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 2,
    };
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Discover a new type of furniture, that uses :wateressence: and :fireessence: to generate :mana:.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t('Unlock furniture "Mana Steamer"');
  }
}

const manaSteamers = new ManaSteamers();

EnhancementPurchaseListeners.register((state, enhancement) => {
  if (enhancement == manaSteamers) {
    return clearVisibleSpellsCache(state);
  }
  return state;
}, manaSteamers.getId());

export { manaSteamers };
