import { getEventMessageById } from "./events/Events";
import { GameState } from "./GameState";
import { hasTemporaryEffect } from "./temporaryeffects/TemporaryEffects";

export function cleanup(state: GameState): GameState {
  // Cleanup events
  const events = Object.values(state.run.eventOccurrences).sort(
    (a, b) => b.timestamp - a.timestamp,
  );
  const terminalEvents = events.filter((event) => {
    return (
      event.currentMessageId &&
      getEventMessageById(event.currentMessageId).isTerminal()
    );
  });
  const oldEventOccurrenceIds = terminalEvents
    .slice(20)
    .map((eventOccurrence) => eventOccurrence.eventOccurrenceId);
  oldEventOccurrenceIds.forEach((eventOccurrenceId) => {
    delete state.run.eventOccurrences[eventOccurrenceId];
  });

  // Cleanup old temporary effect
  for (const key in state.run.temporaryEffects) {
    const temporaryEffect = state.run.temporaryEffects[key];
    if (
      !hasTemporaryEffect(state, temporaryEffect.id, temporaryEffect.target)
    ) {
      delete state.run.temporaryEffects[key];
    }
  }

  state.global.notifications = state.global.notifications.filter(
    (notification) => notification.globalTimestampRead == null,
  );

  // Cleanup old action results
  state.run.exploration.actionResultQueue =
    state.run.exploration.actionResultQueue.slice(-10);

  return state;
}
