import { t } from "../../../../utils/I18n";
import { createChestEvent } from "../../../events/autorandom/Chest";
import { ExplorationTriggerGroup } from "../../../events/autorandom/ExplorationTrigger";
import { GameState } from "../../../GameState";
import { FeatheredHat } from "../../../items/equipment/accessory/air/FeatheredHat";
import { StoneMail } from "../../../items/equipment/body/earth/StoneMail";
import { WizardsRobe } from "../../../items/equipment/body/water/WizardsRobe";
import { glaive } from "../../../items/equipment/hand/air/Glaive";
import { bastardSword } from "../../../items/equipment/hand/earth/BastardSword";
import { BattleAxe } from "../../../items/equipment/hand/fire/BattleAxe";
import { HandAxe } from "../../../items/equipment/hand/fire/HandAxe";
import { wandOfWater } from "../../../items/equipment/hand/water/WandOfWater";
import { HealthPotion } from "../../../items/equipment/pouch/HealthPotion";
import Wildgrass from "../../../items/material/Wildgrass";
import { SpellElement } from "../../../spells/Elements";
import { Dungeon } from "../Dungeon";
import { DungeonFloor } from "../DungeonFloor";
import TrainingTowerAir from "./TrainingTowerAir";
import TrainingTowerEarth from "./TrainingTowerEarth";
import TrainingTowerFire from "./TrainingTowerFire";
import TrainingTowerWater from "./TrainingTowerWater";

class TrainingTowers extends Dungeon {
  getId(): string {
    return "trainingTowers";
  }

  getName(): string {
    return t("Training Towers");
  }

  getDungeonFloors(): DungeonFloor[] {
    return [
      TrainingTowerFire,
      TrainingTowerEarth,
      TrainingTowerWater,
      TrainingTowerAir,
    ];
  }

  isUnlocked(state: GameState): boolean {
    return true;
  }
}

export const FLOOR_FOR_ELEMENT: Partial<Record<SpellElement, DungeonFloor>> = {
  Fire: TrainingTowerFire,
  Earth: TrainingTowerEarth,
  Water: TrainingTowerWater,
  Air: TrainingTowerAir,
};

export const trainingTowers = new TrainingTowers();

class TrainingTowersExplorationGroup extends ExplorationTriggerGroup {
  getHintMessageWhenPossible(): string {
    return t("Explore the Training Towers");
  }
  getHintMessageWhenNotPossible(): string {
    return t(
      "This Storyline can be found by exploring the Training Towers, but you cannot get it this run",
    );
  }
  maxTriggersPerRun(): number {
    return 2;
  }
  getId(): string {
    return "trainingTowers";
  }
  getExplorationSuccessesBase(): number {
    return 110;
  }
  getDungeonFloors(): DungeonFloor[] {
    return trainingTowers.getDungeonFloors();
  }
}

export const trainingTowersExplorationGroup =
  new TrainingTowersExplorationGroup();

export function loadTrainingTowerChests() {
  createChestEvent(
    "trainingTowersChestCommon1",
    [
      TrainingTowerFire.getId(),
      TrainingTowerEarth.getId(),
      TrainingTowerWater.getId(),
      TrainingTowerAir.getId(),
    ],
    40,
    [
      {
        itemOccurrence: { itemId: Wildgrass.getId(), params: {} },
        amount: 10,
      },
    ],
  );

  createChestEvent(
    "trainingTowersChestUncommon2",
    [
      TrainingTowerFire.getId(),
      TrainingTowerEarth.getId(),
      TrainingTowerWater.getId(),
      TrainingTowerAir.getId(),
    ],
    10,
    [
      {
        itemOccurrence: { itemId: HealthPotion.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "fireTrainingTowersChestUncommon",
    [TrainingTowerFire.getId()],
    18,
    [
      {
        itemOccurrence: { itemId: HandAxe.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "fireTrainingTowersChestRare",
    [TrainingTowerFire.getId()],
    9,
    [
      {
        itemOccurrence: { itemId: BattleAxe.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "earthTrainingTowersChestUncommon",
    [TrainingTowerEarth.getId()],
    18,
    [
      {
        itemOccurrence: { itemId: StoneMail.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "earthTrainingTowersChestRare",
    [TrainingTowerEarth.getId()],
    10,
    [
      {
        itemOccurrence: { itemId: bastardSword.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "waterTrainingTowersChestUncommon",
    [TrainingTowerWater.getId()],
    15,
    [
      {
        itemOccurrence: { itemId: WizardsRobe.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "waterTrainingTowersChestRare",
    [TrainingTowerWater.getId()],
    5,
    [
      {
        itemOccurrence: { itemId: wandOfWater.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "airTrainingTowersChestUncommon",
    [TrainingTowerAir.getId()],
    12,
    [
      {
        itemOccurrence: { itemId: FeatheredHat.getId(), params: {} },
        amount: 1,
      },
    ],
  );

  createChestEvent(
    "airTrainingTowersChestRare",
    [TrainingTowerAir.getId()],
    6,
    [
      {
        itemOccurrence: { itemId: glaive.getId(), params: {} },
        amount: 1,
      },
    ],
  );
}
