import { t } from "../../../../utils/I18n";
import { getAccumulatorVariable } from "../../../AccumulatorVariables";
import GoblinChief from "../../../exploration/enemies/sewers/GoblinChief";
import {
  addEnemyDeathListener,
  startCombat,
} from "../../../exploration/Exploration";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { addUICommand } from "../../../UICommands";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

const goblinDenStoryline = new Storyline(
  "goblinDen",
  t("Goblin Den"),
  240,
  20,
  t("Defeat at least 80 Goblins of any kind in the Sewers this retirement"),
  t("+40:land:"),
  (_state) => true,
);

export function loadGoblinDen() {
  Storylines.register(goblinDenStoryline);

  const goblinDenEventBuilder = buildEvent(
    "goblinDen",
    t("(Storyline) Goblin Den"),
    [EventTag.STORYLINE],
  ).setStorylineIds([goblinDenStoryline.getId()]);

  goblinDenEventBuilder.message(
    t(`"There are a lot of Goblins down here!"
    
You hear that from your hired wizard, who has been accompanying you through the disgusting traversal of the Sewers. You agree. You have seen - and felled - quite a few; you wonder if they might be nesting in this place.

While you think about this, you spot a room to the side of the tunnel you are walking through. It is half open, and light is seeping out of the door. You decide to peek inside... and you end up finding something very interesting.`),
  );
  goblinDenEventBuilder
    .message(
      t(
        `The room looks like a makeshift camp. There are a few crude tents laid on the ground, and there is an active bonfire in the center, which illuminates the room. You spot a large figure towards the back - it is a Goblin, but it is larger than the others. It appears to be quite dangerous.
      
You show the room to your hired wizard, and then she asks you in whispers: "Should we eliminate it and inspect the room?"

The prospect of loot tempts you, though it's going to be a tough fight... you want to make sure you are ready first.`,
      ),
    )
    .option(t("Fight the Goblin Chief"), {
      transform: (state) => {
        state = addUICommand(state, "goToExploration");
        state = addUICommand(state, "closeAllMenus");
        state = addUICommand(state, "closeEventOverlay");
        state = startCombat(GoblinChief)(state);
        return state;
      },
    })
    .option(t("Maybe later"))
    .end();

  goblinDenEventBuilder
    .message(
      t(`The Goblin Chief falls to the ground, unconscious, as you manage to succeed and overpower it. You somehow feel bad for the other Goblins - you decide to refrain from killing it.
      
But you *are* going to get a helping out of the loot of this room.`),
    )
    .tag("afterFight");

  goblinDenEventBuilder.message(
    t(`You search everywhere - inside the tents, around the room, even the Goblin Chief itself. Very disappointingly, though, you find nothing of value. The hired wizard also looks disappointed.
      
But you did find something. It's a ladder on the back side of the room, which goes up towards the ceiling. And on the ceiling, there is a trap door with a safety lock. You decide to check it out - you carefully climb the ladder, undo the safety lock, and push the trap door upwards. Then you carefully peek through the hole.

What you see, however, *really* surprises you. *It is your own home's basement!*`),
  );

  goblinDenEventBuilder
    .message(
      t(`Without believing your eyes, you go back up and scream at the hired wizard to follow you. She does so - and after seeing the other side, she becomes just as dumbfounded as you.
      
Then your mind begins to race. *You had a Goblin Den right below your basement, this entire time.* You were not able to see it because it was locked from the other side - but it meant those goblins could have *come in*!

You can't let that happen. You race back down, push the Goblin Chief out of the door of the room, and then lock it - from the inside. You check one more time for any other Goblins, but there are none - and you breathe a sigh of relief.`),
    )
    .option(
      t("Continue"),
      { transform: (state) => goblinDenStoryline.complete(state) },
      "end",
    );

  goblinDenEventBuilder
    .message(
      t(`Then you start thinking about what this also means. This means your basement is actually connected to an *extra room*. But - as you look around - the room is entirely unusable right now. It will take a lot of time to clean this room up, get rid of all of the things the Goblins set in this room, and more cleanly separate it from the rest of the Sewers.
  
You don't have time to do that, not right now. But at some point, you tell yourself, you should - you jot down some notes in your diary, hoping you (or at least someone else!) will remember about it...

**You have completed the "Goblin Den" Storyline! In future runs, the extra room will give you +40:land:!**`),
    )
    .tag("end")
    .end();

  const goblinDenEvent = goblinDenEventBuilder.build();

  addEnemyDeathListener(goblinDenEvent.getId() + "Triggerer", (state) => {
    if (
      !goblinDenEvent.hasTriggered(state) &&
      !goblinDenStoryline.isCompleted(state) &&
      getAccumulatorVariable(state, "goblinKills") >= 80
    ) {
      return triggerEvent(goblinDenEvent)(state);
    }
    return state;
  });

  registerTransformation(
    [[TransformationTags.Land, TransformationTags.Cap]],
    "goblinDen",
    goblinDenStoryline.getName(),
    TransformationType.Addition,
    (state) => (goblinDenStoryline.isBonusActive(state) ? 40 : 0),
  );
}

export { goblinDenStoryline };
