import { t } from "../../../../utils/I18n";
import { getBestiaryVisibleEnemies } from "../../../exploration/enemies/Enemy";
import { addGameStateListener } from "../../../GameStateListeners";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { BookOfMonsters } from "./BookOfMonstersStoryline";

export function loadBestiaryUnlocker() {
  Storylines.register(BookOfMonsters);
  const bestiaryUnlockEventBuilder = buildEvent(
    "bookOfMonsters",
    t("(Storyline) Compendium"),
    [EventTag.STORYLINE],
  ).setStorylineIds(BookOfMonsters.getId());

  bestiaryUnlockEventBuilder.message(
    t(`You fell one more enemy as you explore. You look behind you and you notice a familiar figure - it is your hired wizard! What is she doing all the way here?
    
"That was *so cool*", she exclaims loudly, after you notice her. "You are strong! How do you do it?"

You are quite startled and surprised by her presence, and are not quite sure how to respond, but she continues before you are able to...`),
  );
  bestiaryUnlockEventBuilder
    .message(
      t(`"I've been actually watching you since you started exploring, and - I've noticed the various kinds of enemies you have been fighting and items you have been finding. And I've been *recording information* about them!" She shows you a book, titled *"Compendium"*.
      
You admit you are a little intrigued. You walk up to her and open the book. It is barely filled in, but the few pages that have writing in them have quite a lot of information - including even *drawings* of each of the enemies you have defeated!`),
    )
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = BookOfMonsters.complete(state);
          state = BookOfMonsters.activateBonus(state);
          return state;
        },
      },
      "end",
    );
  bestiaryUnlockEventBuilder
    .message(
      t(`You are pretty impressed. Some of the enemy entries even have observations from her on how to combat them effectively!
      
Something like this might justify her accompanying you. You hesitate to tell her immediately - you sense she will get extremely excited. For a brief moment you even think about her motivations. You tell yourself it's not the time right now, though. Resolved to let her know that she should come with you on your explorations *after* you go back home, you thank her for starting the book - you are sure it'll be useful.

**You have completed the "Compendium" Storyline! You have permanently unlocked the "Compendium" screen, which records information about items you have found, and about enemies you have defeated or battled before. This will persist even after retirement!**`),
    )
    .tag("end")
    .end();

  const bestiaryUnlockEvent = bestiaryUnlockEventBuilder.build();

  addGameStateListener("bestiaryUnlockListener", (state) => {
    if (
      !bestiaryUnlockEvent.hasTriggered(state) &&
      !BookOfMonsters.isCompleted(state) &&
      getBestiaryVisibleEnemies(state).length >= 5
    ) {
      return triggerEvent(bestiaryUnlockEvent);
    }
  });
}
