export enum SpellElement {
  Fire = "Fire",
  Earth = "Earth",
  Water = "Water",
  Air = "Air",
  Poison = "Poison",
  Mind = "Mind",
  Life = "Life",
  Electric = "Electric",
  Death = "Death",
  Holy = "Holy",
  Time = "Time",
  Space = "Space",
}

export function translateElement(element: SpellElement): string {
  switch (element) {
    case SpellElement.Fire:
      return t("Fire");
    case SpellElement.Earth:
      return t("Earth");
    case SpellElement.Air:
      return t("Air");
    case SpellElement.Water:
      return t("Water");
    case SpellElement.Poison:
      return t("Poison");
    case SpellElement.Mind:
      return t("Mind");
    case SpellElement.Life:
      return t("Life");
    case SpellElement.Electric:
      return t("Electric");
    case SpellElement.Death:
      return t("Death");
    case SpellElement.Holy:
      return t("Holy");
    case SpellElement.Time:
      return t("Time");
    case SpellElement.Space:
      return t("Space");
    // Do not add a default
  }
}

export function getRankForLevel(level: number): string {
  if (level >= 90) {
    const rankPlus = Math.floor((level - 80) / 10.0);
    return t("Eminence +{{rank}}", { rank: formatNumber(rankPlus) });
  }
  if (level >= 80) return t("Eminence");
  //if (level >= 75) return t("Grandmaster");
  if (level >= 70) return t("Master");
  //if (level >= 65) return t("Archmage");
  if (level >= 60) return t("Archmage");
  //if (level >= 55) return t("High Expert");
  if (level >= 50) return t("Expert");
  //if (level >= 45) return t("High Magus");
  if (level >= 40) return t("Magus");
  //if (level >= 35) return t("Instructor");
  if (level >= 30) return t("Adept");
  //if (level >= 25) return t("Mage");
  if (level >= 20) return t("Apprentice");
  //if (level >= 15) return t("Journeyman");
  if (level >= 10) return t("Novice");
  //if (level >= 5) return t("Novice");
  return t("Initiate");
}

export const ELEMENT_LEVEL_CAP_BASE = 80;
export const ELEMENT_LEVEL_CAP_DEMO_BASE = 30;

const expTable = new Map<number, Map<number, number>>();

export function getElementExp(state: GameState, element: SpellElement): number {
  return state.run.elementExperience?.[element] || 0;
}

const maxElementLevelTags = [TransformationTags.MaxElementLevel];

export function getMaxElementLevel(state: GameState): number {
  return applyTransformationsPermaCached(
    maxElementLevelTags,
    state,
    isDemo() ? ELEMENT_LEVEL_CAP_DEMO_BASE : ELEMENT_LEVEL_CAP_BASE,
  );
}

const getIncrementalExpRequiredForLevelAndExponent = memoize(
  (level: number, exponent: number, maxLevel: number): number => {
    if (level <= 1) {
      return 0;
    }
    if (level > maxLevel) {
      return Infinity;
    }
    return Math.floor(600 * Math.pow(exponent, level - 2));
  },
);

export const getTotalExpRequiredForLevelByExponent = (
  level: number,
  exponent: number,
  maxLevel: number,
): number => {
  if (level <= 1) {
    return 0;
  }
  if (level > maxLevel) {
    return Infinity;
  }
  const cachedExp = expTable.get(exponent)?.get(level);
  if (cachedExp != null) {
    return cachedExp;
  }
  return (
    getTotalExpRequiredForLevelByExponent(level - 1, exponent, maxLevel) +
    getIncrementalExpRequiredForLevelAndExponent(level, exponent, maxLevel)
  );
};

export const getLevelByExpAndExponent = (
  exp: number,
  exponent: number,
  maxLevel: number,
  estimateLevel?: number,
): number => {
  if (estimateLevel != null) {
    if (
      getTotalExpRequiredForLevelByExponent(
        estimateLevel,
        exponent,
        maxLevel,
      ) <= exp &&
      getTotalExpRequiredForLevelByExponent(
        estimateLevel + 1,
        exponent,
        maxLevel,
      ) > exp
    ) {
      return estimateLevel;
    }
  }
  let level = 1;
  while (
    getTotalExpRequiredForLevelByExponent(level + 1, exponent, maxLevel) <= exp
  ) {
    level += 1;
  }
  return level;
};

function getExponentForElement(
  state: GameState,
  element: SpellElement,
): number {
  if (
    state.run.primaryElement != null &&
    state.run.elementExponents?.[element] == null
  ) {
    if (!getUnlockedElements(state).includes(element)) {
      return 2;
    }
    const primary =
      state.run.primaryElement === element ||
      state.run.secondaryElement === element;
    const exponent = applyTransformations(
      [TransformationTags.ExpRequirementScale, element],
      state,
      primary ? 1.38 : isPartiallyUnlocked(state, element) ? 2 : 1.43,
      { element: element },
    );
    state.run.elementExponents[element] = exponent;
  }
  return state.run.elementExponents[element] ?? 1.43;
}

export function getIncrementalExpRequiredForLevel(
  state: GameState,
  level: number,
  element: SpellElement,
): number {
  const exponent = getExponentForElement(state, element);
  return getIncrementalExpRequiredForLevelAndExponent(
    level,
    exponent,
    getMaxElementLevel(state),
  );
}

export function getTotalExpRequiredForLevel(
  state: GameState,
  level: number,
  element: SpellElement,
): number {
  const exponent = getExponentForElement(state, element);
  return getTotalExpRequiredForLevelByExponent(
    level,
    exponent,
    getMaxElementLevel(state),
  );
}

function getLevelByExp(
  state: GameState,
  exp: number,
  element: SpellElement,
  estimateLevel?: number,
): number {
  const exponent = getExponentForElement(state, element);
  return getLevelByExpAndExponent(
    exp,
    exponent,
    getMaxElementLevel(state),
    estimateLevel,
  );
}

const estimateLevels: Partial<Record<SpellElement, number>> = {};

export function getElementLevel(
  state: GameState,
  element: SpellElement,
): number {
  return cacheByTickCountElementLevel(state, element, (state) => {
    const estimateLevel = estimateLevels[element];
    const level = getLevelByExp(
      state,
      state.run.elementExperience?.[element] || 0,
      element,
      estimateLevel,
    );
    estimateLevels[element] = level;
    return level;
  });
}

function grantElementExpImpl(
  element: SpellElement,
  amount: number,
  state: GameState,
) {
  let current = getElementExp(state, element);
  current += amount;
  current = Math.max(current, 0);
  state.run.elementExperience[element] = current;
  return state;
}

export const grantElementExp = memoizeWithLimit(
  (element, amount) => grantElementExpImpl.bind(this, element, amount),
  10000,
);

export function getTotalElementExp(state: GameState): number {
  let totalExp = 0;
  for (let element in state.run.elementExperience) {
    totalExp += getElementExp(state, element as SpellElement);
  }
  return totalExp;
}

function selectPrimaryElementImpl(element: SpellElement, state: GameState) {
  state.run.primaryElement = element;
  return state;
}

export function selectPrimaryElement(
  this: any,
  element: string,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return selectPrimaryElementImpl.bind(this, element);
}

function selectSecondaryElementImpl(element: SpellElement, state: GameState) {
  state.run.secondaryElement = element;
  return state;
}

export function selectSecondaryElement(
  this: any,
  element: string,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return selectSecondaryElementImpl.bind(this, element);
}

export function getPrimaryElements(state: GameState): SpellElement[] {
  if (state.run.secondaryElement != null && state.run.primaryElement != null) {
    return [state.run.primaryElement, state.run.secondaryElement];
  }
  if (state.run.primaryElement != null) {
    return [state.run.primaryElement];
  }
  return [];
}

export function getPrimaryElementMaxLevelForRun(state: GameState): number {
  const primaryElements = getPrimaryElements(state);
  return Math.max(
    ...primaryElements.map((element) => getElementLevel(state, element)),
    1,
  );
}

// Includes partially unlocked elements
export function getUnlockedElements(state: GameState): SpellElement[] {
  let value = state.temp.unlockedElementsCache;
  if (value == null) {
    const elementValues = valuesOf(SpellElement);
    value = keysOf(state.world.unlockedElements)
      .filter((key) => state.world.unlockedElements[key])
      .sort((a, b) => elementValues.indexOf(a) - elementValues.indexOf(b))
      .concat(
        keysOf(state.run.partiallyUnlockedElements).filter(
          (key) =>
            state.run.partiallyUnlockedElements[key] &&
            !state.world.unlockedElements[key],
        ),
      );
    state.temp.unlockedElementsCache = value;
  }
  return value;
}

export function getFullyUnlockedElements(state: GameState): SpellElement[] {
  return keysOf(state.world.unlockedElements).filter(
    (key) => state.world.unlockedElements[key],
  );
}

export function isPartiallyUnlocked(
  state: GameState,
  element: string,
): boolean {
  return (
    !(state.world.unlockedElements[element] ?? false) &&
    (state.run.partiallyUnlockedElements[element] ?? false)
  );
}

function unlockElementImpl(element: string, state: GameState) {
  state.world.unlockedElements[element] = true;
  state.temp.unlockedElementsCache = null;
  state.temp.spellRequirementsCache = {};
  state = clearVisibleSpellsCache(state);
  return state;
}

export function unlockElement(this: any, element: string): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return unlockElementImpl.bind(this, element);
}

function partiallyUnlockElementImpl(element: string, state: GameState) {
  state.run.partiallyUnlockedElements[element] = true;
  state = setWorldFlagForNextRun("everPartiallyUnlockedElement_" + element)(
    state,
  );
  state.temp.unlockedElementsCache = null;
  state.temp.spellRequirementsCache = {};
  return state;
}

export function partiallyUnlockElement(
  this: any,
  element: string,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return partiallyUnlockElementImpl.bind(this, element);
}

export function hasElementBeenPartiallyUnlockedInPreviousRun(
  state: GameState,
  element: SpellElement,
): boolean {
  return hasWorldFlag(state, "everPartiallyUnlockedElement_" + element);
}

export function getMaxPrimaryElementLevel(
  state: GameState,
  element: SpellElement,
): number {
  return Math.min(
    state.world.maxPrimaryElementLevels?.[element] || 0,
    getMaxElementLevel(state),
  );
}

export function getMaxOfMaxPrimaryElementLevels(state: GameState): number {
  const maxElementLevels = state.world.maxPrimaryElementLevels || {};
  let max = 0;
  for (let element in maxElementLevels) {
    max = Math.min(
      Math.max(max, maxElementLevels[element as SpellElement] || 0),
      getMaxElementLevel(state),
    );
  }
  return max;
}

export function getSumOfMaxPrimaryElementLevels(state: GameState): number {
  const maxElementLevels = state.world.maxPrimaryElementLevels || {};
  let total = 0;
  for (let element in maxElementLevels) {
    total += Math.min(
      (maxElementLevels[element as SpellElement] ?? 1) - 1,
      getMaxElementLevel(state) - 1,
    );
  }
  return total;
}

const OTHER_ELEMENT_GRANT = 0.1;

const ELEMENT_EXP_POW_BASE = 1.03;
const BASE_EXP_MULTIPLIER = 1.0;

export const MANA_SPENT_EXP_EXPONENT = 0.3;

const tagsForExpGain = memoize((tags, element) => [
  ...tags,
  element,
  TransformationTags.ExpGain,
]);

export function getElementExpByUsage(
  state: GameState,
  element: SpellElement,
  level: number,
  manaSpent: number,
  isSpellElement: boolean,
  tags: string[] = [],
): number {
  const transformed = applyTransformations(
    tagsForExpGain(tags, element),
    state,
    Math.pow(ELEMENT_EXP_POW_BASE, Math.max(level - 1, 0)) *
      Math.pow(manaSpent, MANA_SPENT_EXP_EXPONENT) *
      BASE_EXP_MULTIPLIER *
      (isSpellElement ? 1.0 : OTHER_ELEMENT_GRANT),
    { school: element, level: level },
  );
  if (transformed == 0) {
    return 0;
  }
  return Math.max(5.0, transformed);
}

const BASE_STARTING_EXP = 400;
const STARTING_EXP_POWER_BASE = 1.27;
const STARTING_ELEMENT_EXP_BASE_TAGS_BY_ELEMENT: Record<
  SpellElement,
  TransformationTag[]
> = fromEntries(
  valuesOfCached(SpellElement).map((element) => [
    element,
    [TransformationTags.StartingElementExpBase, element],
  ]),
);

export function startingExpForElement(state: GameState, element: SpellElement) {
  return (
    BASE_STARTING_EXP *
    (Math.pow(
      applyTransformationsPermaCached(
        STARTING_ELEMENT_EXP_BASE_TAGS_BY_ELEMENT[element],
        state,
        STARTING_EXP_POWER_BASE,
      ),
      getMaxPrimaryElementLevel(state, element),
    ) -
      1)
  );
}

export function maximumElementLevelExpMultiplier(state: GameState) {
  return cacheIndefinitely(
    state,
    (s) => "maximumElementLevelExpMultiplier",
    (s) => {
      const maxLevel = Math.max(getMaxOfMaxPrimaryElementLevels(s), 1);
      const sumOfLevels = getSumOfMaxPrimaryElementLevels(s);
      const multiplier =
        (1 + (Math.pow(1.11, maxLevel - 1) - 1) / 2.5) *
        (Math.pow(sumOfLevels + Math.pow(20, 1 / 0.8), 0.8) / 20);
      return multiplier;
    },
  );
}

export function maximumElementLevelProductionMultiplier(
  state: GameState,
  multiplier: number,
) {
  return multiplier * maximumElementLevelProductionMultiplierDx(state);
}

export function maximumElementLevelProductionMultiplierDx(state: GameState) {
  return cacheIndefinitely(
    state,
    (s) => "maximumElementLevelProductionMultiplierDx",
    (s) => {
      const researchMultiplier = maximumElementLevelExpMultiplier(state);
      return 1 + (Math.pow(researchMultiplier, 0.4) - 1) / 3.2;
    },
  );
}

registerTransformation(
  [[TransformationTags.ExpGain]],
  "MELExp",
  t("Maximum Element Level multiplier"),
  TransformationType.Multiplier,
  maximumElementLevelExpMultiplier,
);

registerTransformation(
  [[TransformationTags.Production]],
  "MELProduction",
  t("Maximum Element Level production multiplier"),
  TransformationType.Multiplier,
  maximumElementLevelProductionMultiplierDx,
);

registerTransformation(
  [[TransformationTags.ChannelingEfficiency]],
  "MELChanneling",
  t("Maximum Element Level channeling multiplier"),
  TransformationType.Multiplier,
  maximumElementLevelProductionMultiplierDx,
);

registerTransformation(
  [[TransformationTags.Loot]],
  "MELLoot",
  t("Maximum Element Level loot multiplier"),
  TransformationType.Multiplier,
  maximumElementLevelProductionMultiplierDx,
);

registerTransformation(
  [[TransformationTags.FamiliarExp]],
  "MELFamiliarExp",
  t("Maximum Element Level familiar Exp multiplier"),
  TransformationType.Multiplier,
  maximumElementLevelProductionMultiplierDx,
);

Object.keys(SpellElement).forEach((element) => {
  registerTransformation(
    [
      [
        TransformationTags.Cost,
        element,
        TransformationTags.Spell,
        Resource.Mana,
      ],
    ],
    element + "SpellCostReducer",
    t("Primary Element"),
    TransformationType.Multiplier,
    (state) =>
      getPrimaryElements(state).includes(element as SpellElement) ? 0.5 : 1,
  );
});

function retirementListener(state: GameState): GameState {
  const primaryElement = state.run.primaryElement;
  if (primaryElement) {
    const level = getElementLevel(state, primaryElement);
    state.world.maxPrimaryElementLevels[primaryElement] = Math.max(
      getMaxPrimaryElementLevel(state, primaryElement),
      level,
    );
  }
  const secondaryElement = state.run.secondaryElement;
  if (secondaryElement) {
    const level = getElementLevel(state, secondaryElement);
    state.world.maxPrimaryElementLevels[secondaryElement] = Math.max(
      getMaxPrimaryElementLevel(state, secondaryElement),
      level,
    );
  }
  return state;
}

registerRetirementListener("mpl", retirementListener);

export function grantStartingExp(state: GameState): GameState {
  for (let element of keysOf(state.world.maxPrimaryElementLevels)) {
    const exp = startingExpForElement(state, element);
    state = grantElementExp(element, exp)(state);
    state.run.lastComputedLevelUpElementExperience[element] = exp;
  }
  return state;
}

function maxElementLevelsOnPrevRetirementAssigner(state: GameState): GameState {
  state.run.maxElementLevelsOnPrevRetirement = clone(
    state.world.maxElementLevels,
  );
  return state;
}

registerPostRetirementListener(
  "maxElementLevelsOnPrevRetirement",
  maxElementLevelsOnPrevRetirementAssigner,
);

export function meetsRequirements(
  state: GameState,
  requirements: Partial<Record<SpellElement, number>>,
): boolean {
  if (
    Object.keys(requirements).some((elementName) => {
      const levelRequirement = requirements[elementName as SpellElement] || 0;
      if (levelRequirement <= 0) {
        return false;
      }
      return (
        getElementLevel(state, elementName as SpellElement) <
          (requirements[elementName as SpellElement] || 0) ||
        !getUnlockedElements(state).includes(elementName as SpellElement)
      );
    })
  ) {
    return false;
  }
  return true;
}

const cachedExponents = [1.38, 1.43, 2];
cachedExponents.forEach((exponent) => expTable.set(exponent, new Map()));

for (let level = 1; level < 500; level++) {
  cachedExponents.forEach((exponent) => {
    expTable
      .get(exponent)
      ?.set(level, getTotalExpRequiredForLevelByExponent(level, exponent, 499));
  });
}
