import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { RESOURCE_FOR_ELEMENT } from "./channeling/ChannelingResources";
import {
  getElementLevel,
  getPrimaryElements,
  isPartiallyUnlocked,
  SpellElement,
  translateElement,
} from "./Elements";
import { Spell } from "./Spell";

export const PARTIALLY_UNLOCKED_STORAGE_MULTIPLIER = 0.25;
export const PARTIALLY_UNLOCKED_CHANNELING_MULTIPLIER = 1.0 / 3.0;
export const LEVELS_PER_RANK = 10.0;
const EXPONENTIAL_BONUS_PER_RANK = 1.33353;

export function getRankMultiplier(
  baseLevel: number,
  currentLevel: number,
): number {
  const rankDifference = getRankDifference(baseLevel, currentLevel);
  if (rankDifference <= 0) {
    return 1.0;
  }
  const powValue = Math.pow(EXPONENTIAL_BONUS_PER_RANK, rankDifference);
  return powValue;
}

export function getRankDifference(
  baseLevel: number,
  currentLevel: number,
): number {
  const rank = Math.floor(currentLevel / LEVELS_PER_RANK);
  const spellRank = Math.floor(baseLevel / LEVELS_PER_RANK);
  const rankDifference = rank - spellRank;
  return rankDifference;
}

export function loadElementMultipliers() {
  for (let element of valuesOf(SpellElement)) {
    registerTransformation(
      [
        [
          TransformationTags.Spell,
          TransformationTags.ActionEffect,
          TransformationTags.ActionMagnitude,
          element,
        ],
        [
          TransformationTags.Spell,
          TransformationTags.ActionEffect,
          TransformationTags.TemporaryEffectMagnitude,
          element,
        ],
        [
          TransformationTags.Spell,
          TransformationTags.ActionEffect,
          TransformationTags.TemporaryEffectExtraCostDivisor,
          element,
        ],
      ],
      "elementLevel" + element,
      t("{{element}} Rank", { element: translateElement(element) }),
      TransformationType.Multiplier,
      (state, params) => {
        const spell = params.action as Spell;
        const baseLevel = spell.getLevelRequirements()[element];
        if (baseLevel == null) {
          return 1.0;
        }
        const currentLevel = getElementLevel(state, element);
        return getRankMultiplier(baseLevel, currentLevel);
      },
    );

    registerTransformation(
      [
        [
          TransformationTags.Spell,
          TransformationTags.ActionEffect,
          TransformationTags.TemporaryEffectExtraCost,
          element,
        ],
      ],
      "elementLevelCost" + element,
      t("{{element}} Rank", { element: translateElement(element) }),
      TransformationType.Multiplier,
      (state, params) => {
        const spell = params.action as Spell;
        const baseLevel = spell.getLevelRequirements()[element];
        if (baseLevel == null) {
          return 1.0;
        }
        const currentLevel = getElementLevel(state, element);
        const multiplier = getRankMultiplier(baseLevel, currentLevel);
        return 1.0 / ((multiplier + 1.0) / 2);
      },
    );

    registerTransformation(
      [[element, TransformationTags.ExpGain]],
      element + "PrimaryElementMultiplier",
      t("Primary Element"),
      TransformationType.Multiplier,
      (state) => (getPrimaryElements(state).includes(element) ? 2.0 : 1.0),
    );

    registerTransformation(
      [[element, TransformationTags.ChannelingEfficiency]],
      element + "ChannelingMultiplier",
      t("{{element}} Element Rank", { element: translateElement(element) }),
      TransformationType.Multiplier,
      (state) => {
        const currentLevel = getElementLevel(state, element);
        return getRankMultiplier(1, currentLevel);
      },
    );

    registerTransformation(
      [[RESOURCE_FOR_ELEMENT[element], TransformationTags.PerBuildingCap]],
      element + "PartiallyUnlockedCapMultiplier",
      t("Partially Unlocked"),
      TransformationType.Multiplier,
      (state) =>
        isPartiallyUnlocked(state, element)
          ? PARTIALLY_UNLOCKED_STORAGE_MULTIPLIER
          : 1.0,
    );

    registerTransformation(
      [
        [element, TransformationTags.ChannelingEfficiency],
        [element, TransformationTags.ExpGain],
      ],
      element + "PartiallyUnlockedChannelingMultiplier",
      t("Partially Unlocked"),
      TransformationType.Multiplier,
      (state) =>
        isPartiallyUnlocked(state, element)
          ? PARTIALLY_UNLOCKED_CHANNELING_MULTIPLIER
          : 1.0,
    );
  }
}
