import memoize from "sonic-memoize";
import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { hasFlag } from "../Flags";
import { GameState } from "../GameState";
import {
  applyTransformations,
  explainTransformationsText,
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { AttackTarget } from "./AttackTarget";

export enum CombatStat {
  MaxHP = "MaxHP",
  Attack = "Attack",
  AttackDelay = "AttackDelay",
  Defense = "Defense",
  HPRegen = "HPRegen",
  Dodge = "Dodge",
  Accuracy = "Accuracy",
  Crit = "Crit",
}

const BASE_VALUES: Record<CombatStat, number> = {
  MaxHP: 60,
  Attack: 5,
  Defense: 0,
  AttackDelay: 2.0,
  HPRegen: 0.0033,
  Dodge: 100,
  Accuracy: 100,
  Crit: 0,
};

export function calculatePlayerMaxHP(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.MaxHP);
}

export function calculatePlayerAttack(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.Attack);
}

export function calculatePlayerAttackDelaySec(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.AttackDelay);
}

export function calculatePlayerDefense(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.Defense);
}

export function calculatePlayerHPRegenRatioPerSec(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.HPRegen);
}

export function calculatePlayerDodgePoints(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.Dodge);
}

export function calculatePlayerAccuracyPoints(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.Accuracy);
}

export function calculatePlayerCritPoints(state: GameState) {
  return calculatePlayerCombatStat(state, CombatStat.Crit);
}

const playerStatTags = memoize((stat) => [AttackTarget.Player, stat]);

export function calculatePlayerCombatStat(state: GameState, stat: CombatStat) {
  return getCachedCombatStat(state, AttackTarget.Player, stat, (state) =>
    applyTransformations(playerStatTags(stat), state, BASE_VALUES[stat]),
  );
}

export function getCachedCombatStat(
  state: GameState,
  target: AttackTarget,
  stat: CombatStat,
  fn: (state: GameState) => number,
  familiarId?: string,
  level?: number,
): number {
  if (target == AttackTarget.Familiar) {
    // Familiars have an extra level of indirection because there are many familiars
    let collection = state.temp.combatStatCache[target][stat];
    if (collection == null) {
      state.temp.combatStatCache[target][stat] = {};
    }
    let value = state.temp.combatStatCache[target][stat][familiarId];
    if (value == null || value.level != level) {
      value = { value: fn(state), level: level };
      state.temp.combatStatCache[target][stat][familiarId] = value;
    }
    return value.value;
  } else {
    let value = state.temp.combatStatCache[target][stat];
    if (value == null) {
      value = fn(state);
      state.temp.combatStatCache[target][stat] = value;
    }
    return value;
  }
}

export function clearCombatStatCache(
  state: GameState,
  target?: AttackTarget,
  stat?: CombatStat,
) {
  if (target != null) {
    if (stat != null) {
      delete state.temp.combatStatCache[target][stat];
    } else {
      for (let stat of valuesOf(CombatStat)) {
        state = clearCombatStatCache(state, target, stat);
      }
    }
  } else {
    for (let target of valuesOf(AttackTarget)) {
      state = clearCombatStatCache(state, target, stat);
    }
  }
  return state;
}

export function explainPlayerCombatStat(
  state: GameState,
  stat: CombatStat,
  explainParams?: Record<string, any>,
) {
  return explainTransformationsText(
    playerStatTags(stat),
    state,
    BASE_VALUES[stat],
    explainParams,
  );
}

registerTransformation(
  [
    [AttackTarget.Player, CombatStat.MaxHP],
    [AttackTarget.Player, CombatStat.HPRegen],
  ],
  "DebugInvincibility",
  t("Debug invincibility"),
  TransformationType.Addition,
  (state) => {
    if (hasFlag(state, "debug_invincibility")) {
      return 99999999;
    }
    return 0;
  },
);

registerTransformation(
  [[AttackTarget.Player, CombatStat.Attack]],
  "DebugInsvincibility",
  t("Debug invincibility attack"),
  TransformationType.Addition,
  (state) => {
    if (hasFlag(state, "debug_invincibility")) {
      return 99999999999;
    }
    return 0;
  },
);
