import { t } from "../../../../utils/I18n";
import { hasFlag, setFlag } from "../../../Flags";
import { addGameStateListener } from "../../../GameStateListeners";
import {
  addToInventory,
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../../../items/Inventory";
import { Items } from "../../../items/Items";
import { getUnlockedElements } from "../../../spells/Elements";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import { calculateIncomePerSecond } from "../../../timetick/IncomeOverTime";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";

export const shardStockpileStoryline = new Storyline(
  "shardStockpile",
  t("Stockpile of Shards"),
  280,
  20,
  t("Obtain 150:mana:/sec after retiring at least 2 times"),
  t("Begin subsequent retirements with 30 shards of every unlocked Element"),
  (state) => true,
);

export function loadShardStockpile() {
  Storylines.register(shardStockpileStoryline);
  const shardStockpileEventBuilder = buildEvent(
    "shardStockpile",
    t("(Storyline) Stockpile of Shards"),
    [EventTag.STORYLINE],
  ).setStorylineIds(shardStockpileStoryline.getId());

  shardStockpileEventBuilder
    .message(
      t(`You have come pretty far, but you still remember how difficult it was to start out. Slow :mana: generation, few or no wizards helping you out, manually crafting all these Shards... Your own inexperience didn't help you, either.
    
You wouldn't wish it on anyone. Now that you feel you are standing on more solid ground, you decide to create a stockpile of shards so this never happens again.

You are going to need a lot of shards...`),
    )
    .option(
      t(
        "Create the stockpile (200 of each of Fire Shard, Earth Shard, Water Shard, Air Shard)",
      ),
      {
        transform: (state) => {
          state = removeFromInventoryWithAnyParams(
            Items.getById("elementalShardFire"),
            200,
            state,
          );
          state = removeFromInventoryWithAnyParams(
            Items.getById("elementalShardEarth"),
            200,
            state,
          );
          state = removeFromInventoryWithAnyParams(
            Items.getById("elementalShardWater"),
            200,
            state,
          );
          state = removeFromInventoryWithAnyParams(
            Items.getById("elementalShardAir"),
            200,
            state,
          );
          state = shardStockpileStoryline.complete(state);
          return state;
        },
        isEnabled: (state) =>
          getTotalAmountOfItem(Items.getById("elementalShardFire"), state) >=
            200 &&
          getTotalAmountOfItem(Items.getById("elementalShardEarth"), state) >=
            200 &&
          getTotalAmountOfItem(Items.getById("elementalShardWater"), state) >=
            200 &&
          getTotalAmountOfItem(Items.getById("elementalShardAir"), state) >=
            200,
      },
      "end",
    )
    .option(t("Maybe later"))
    .end();
  shardStockpileEventBuilder
    .message(
      t(`You put all the shards in a box, neatly divided and sorted. You cover the box, and you safeguard it in a corner of the house. You vow not to open it again - and hope it might help someone. Perhaps your children, if you ever decide to have any...?
      
**You have completed the "Stockpile of Shards" Storyline! In future runs, you will begin with 30 shards of all of your unlocked Elements in your inventory!**`),
    )
    .tag("end")
    .end();

  const shardStockpileEvent = shardStockpileEventBuilder.build();

  addGameStateListener("shardStockpileListener", (state) => {
    if (
      !shardStockpileEvent.hasTriggered(state) &&
      !shardStockpileStoryline.isCompleted(state) &&
      state.world.totalTimesResetted >= 2 &&
      (calculateIncomePerSecond(state)?.Mana ?? 0) >= 150
    ) {
      return triggerEvent(shardStockpileEvent);
    }
  });

  addGameStateListener("shardStockpile", (state) => {
    if (
      shardStockpileStoryline.isBonusActive(state) &&
      !hasFlag(state, "shardStockpileHasGivenShards") &&
      state.run.primaryElement != null
    ) {
      return (state) => {
        if (state.run.primaryElement != null) {
          const unlockedElements = getUnlockedElements(state);
          for (let element of unlockedElements) {
            state = addToInventory(
              { itemId: "elementalShard" + element, params: {} },
              30,
              state,
            );
          }
          state = setFlag("shardStockpileHasGivenShards")(state);
        }
        return state;
      };
    }
  });
}
