import { LevelUpListeners } from "../events/progression/LevelUp";
import { getElementLevel, getUnlockedElements } from "./Elements";

export function loadElementsCommonLogic() {
  LevelUpListeners.register((state, element) => {
    const elements = getUnlockedElements(state);
    const newMaxes = state.world.maxElementLevels;
    let update = false;
    elements.forEach((element) => {
      const previousMax = state.world.maxElementLevels?.[element] ?? 0;
      const newLevel = getElementLevel(state, element);
      if (newLevel > previousMax) {
        newMaxes[element] = newLevel;
        update = true;
      }
    });
    if (update) {
      state.world.maxElementLevels = newMaxes;
    }
    return state;
  }, "maxMaxElementLevels");
}
