import { Entypo } from "@expo/vector-icons";
import { useCallback, useMemo, useRef, useState } from "react";
import { StyleSheet, View } from "react-native";
import { TouchableWithoutFeedback } from "react-native-gesture-handler";
import { useTheme } from "react-native-paper";
import { Ritual } from "../../backend/rituals/Ritual";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import { DescriptionViewer } from "../utility/Miscellaneous";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";

export function RitualDetails(props: { ritual: Ritual }) {
  const state = useGameState();
  const stateRef = useRef(state);
  const ritual = props.ritual;
  const descriptionContents = t(
    `{{ritualDescription}} **{{ritualEffect}}; {{corruptionCost}}:corruption:**`,
    {
      ritualDescription: ritual.getDescription(state),
      ritualEffect: ritual.getEffect(state),
      corruptionCost: formatNumber(ritual.getCorruptionCost(state), {
        showDecimals: true,
      }),
    },
  );

  const isActive = ritual.isActive(state);
  const toggleRitual = useCallback(
    () =>
      stateRef.current.apply((state) =>
        ritual.isActive(state) ? ritual.turnOff(state) : ritual.turnOn(state),
      ),
    [ritual],
  );
  const buttonRow = useMemo(() => {
    if (isActive) {
      return (
        <View style={Styles.flexRowAlignCenter}>
          <Button compact={true} onPress={toggleRitual}>
            {t("Stop Ritual")}
          </Button>
        </View>
      );
    }
    return (
      <View style={Styles.flexRowAlignCenter}>
        <Button compact={true} onPress={toggleRitual}>
          {t("Start Ritual")}
        </Button>
      </View>
    );
  }, [toggleRitual, isActive]);

  return useMemo(
    () => (
      <View style={Styles.mv8}>
        <DescriptionViewer>{descriptionContents}</DescriptionViewer>
        <View style={Styles.mt8}>{buttonRow}</View>
      </View>
    ),
    [descriptionContents, buttonRow],
  );
}

export function RitualView(props: { ritual: Ritual }) {
  const ritual = props.ritual;
  const [collapsed, setCollapsed] = useState(true);
  const theme = useTheme();
  const state = useGameState();
  const name = ritual.getDisplayName(state);
  const toggleCollapsed = useCallback(
    () => setCollapsed(!collapsed),
    [collapsed, setCollapsed],
  );
  return useMemo(
    () => (
      <View style={Styles.pv4}>
        <TouchableWithoutFeedback
          accessibilityRole="button"
          accessibilityHint={t("Expand")}
          onPress={toggleCollapsed}
        >
          <View style={styles.topRowContainer}>
            <Text h2 accessibilityRole="none" style={styles.topContainer}>
              {name}
            </Text>
            <View style={styles.expandContainer}>
              <Entypo
                name={collapsed ? "chevron-right" : "chevron-down"}
                size={24}
                color={theme.colors.onBackground}
              />
            </View>
          </View>
        </TouchableWithoutFeedback>
        {!collapsed && (
          <View style={Styles.ph8}>
            <RitualDetails ritual={ritual} />
          </View>
        )}
      </View>
    ),
    [toggleCollapsed, name, collapsed, theme, ritual],
  );
}

const styles = StyleSheet.create({
  topRowContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    cursor: "pointer",
  },
  expandContainer: {
    width: 40,
  },
  descriptionTextContainer: {
    marginBottom: 8,
    marginTop: 8,
    flex: 1,
    flexDirection: "row",
    flexWrap: "wrap",
  },
  descriptionViewer: { flex: 1, justifyContent: "center" },
  effectContainer: {
    flex: 1,
  },
  effectSubcontainer: {
    flexDirection: "row",
    alignItems: "flex-start",
    flexWrap: "wrap",
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  topContainer: {
    flex: 1,
    marginLeft: 8,
  },
  disabledStorylineText: {
    color: "#aaa",
  },
});
