import memoize from "sonic-memoize";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { SpellElement } from "../../spells/Elements";
import {
  IncomeOverTimeProducer,
  IncomeOverTimeProducers,
  clearCalculatedIncomeCache,
} from "../../timetick/IncomeOverTime";
import {
  applyTransformations,
  explainTransformationsText,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import { BuildingAmountListeners, getBuildingAmount } from "../Buildings";

const BASE_REGEN = 0.75;
const productionTags = [
  TransformationTags.Production,
  Resource.Mana,
  "manaSpout",
];
function getManaRegenPerSec(state: GameState): number {
  return applyTransformations(productionTags, state, BASE_REGEN);
}

class ManaSpout extends Building {
  getId(): string {
    return "manaSpout";
  }

  getName(): string {
    return t("Mana Spout");
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A small piece of furniture that automatically produces Mana. Key for any wizard.",
    );
  }

  effectUnit = memoize(() => ({ unit: t(":mana:/sec") }));

  getDisplayEffect(state: GameState): string {
    return t("^+{{manaRegenPerSec}}^<{{explanation}}>:mana:/sec", {
      manaRegenPerSec: formatNumber(getManaRegenPerSec(state), {
        showDecimals: true,
      }),
      explanation: explainTransformationsText(
        productionTags,
        state,
        BASE_REGEN,
        this.effectUnit(),
      ),
    });
  }

  getBaseLandRequired(): number {
    return 2;
  }

  canTurnOff(): boolean {
    return false;
  }
}

const manaSpout = new ManaSpout();

class BuildManaSpout extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.MANA] };
  }

  getBuilding(): Building {
    return manaSpout;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      WaterEssence: 10,
      AirEssence: 5,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      WaterEssence: 1.25,
      AirEssence: 1.25,
    };
  }

  getBaseManaCost(state: GameState): number {
    return 10;
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.ManaSpoutsUnlocked) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Water;
  }
}

const buildManaSpout = new BuildManaSpout();

IncomeOverTimeProducers.register(
  new IncomeOverTimeProducer(
    manaSpout.getId(),
    manaSpout.getName(),
    (state) => ({
      Mana: getManaRegenPerSec(state) * getBuildingAmount(state, manaSpout),
    }),
  ),
);

BuildingAmountListeners.register((state, building) => {
  if (building == manaSpout) {
    state = clearCalculatedIncomeCache(state);
  }
  return state;
});

export { buildManaSpout as BuildManaSpout, manaSpout as ManaSpout };
