import React, { useCallback, useMemo, useRef } from "react";
import { View } from "react-native";
import {
  EquipmentSlot,
  getEquippedItem,
  getMaxEquippedItemsPerSlot,
} from "../../backend/items/Equipment";
import { Items } from "../../backend/items/Items";
import {
  PouchItem,
  getUsesLeftForPouchItem,
} from "../../backend/items/equipment/PouchItem";
import { t } from "../../utils/I18n";
import { ItemInfo } from "../inventory/ItemInfo";
import { ItemTile } from "../inventory/ItemTile";
import { CheckBox } from "../utility/CheckBox";
import Styles from "../utility/Styles";
import { useGameState } from "../utility/useGameState";

function EquippedPouchSlot(props: { index: number }) {
  const state = useGameState();
  const index = props.index;
  const equippedItem = getEquippedItem(state, EquipmentSlot.Pouch, index);

  const stateRef = useRef(state);
  stateRef.current = state;

  const item =
    equippedItem && (Items.getById(equippedItem.itemId) as PouchItem);
  if (item && !(item instanceof PouchItem)) {
    throw new Error("Only pouch items should be equipped");
  }

  const useAction = item?.getPouchItemAction?.(
    state,
    index,
    equippedItem.params,
  );
  const usesLeft = getUsesLeftForPouchItem(state, index);

  return useMemo(() => {
    if (!equippedItem) {
      return (
        <View key={index}>
          <ItemTile title={t("Empty")} />
        </View>
      );
    }
    return (
      <View key={index}>
        <ItemTile
          icon={item.getId()}
          quality={equippedItem?.params?.itemQuality}
          disabled={!useAction.isEnabled}
          amount={usesLeft > 1 ? usesLeft : undefined}
          title={item.getName(equippedItem.params)}
          onPress={() => stateRef.current.apply(useAction.transform)}
          tooltip={
            <ItemInfo
              showTile={false}
              itemStack={{ itemOccurrence: equippedItem, amount: 1 }}
            />
          }
          tooltipIdSuffix={"explorationPouch" + index}
        />
      </View>
    );
  }, [
    JSON.stringify(equippedItem),
    useAction?.isEnabled,
    index,
    usesLeft,
    useAction?.transform,
    stateRef,
    item?.getName(equippedItem?.params || {}),
  ]);
}

export default function ExplorationPouch(props: {}) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const capacity = getMaxEquippedItemsPerSlot(state, EquipmentSlot.Pouch);
  const slotComponents = useMemo(() => {
    let slotComponents = [];
    for (let i = 0; i < capacity; i++) {
      slotComponents.push(<EquippedPouchSlot key={i} index={i} />);
    }
    return slotComponents;
  }, [capacity]);

  const autoUsePouchToggle = useCallback(() => {
    stateRef.current.apply((state) => {
      state.global.autoUsePouch = !state.global.autoUsePouch;
      return state;
    });
  }, []);

  return (
    <View>
      <View
        style={[
          Styles.flexRowAlignCenter,
          Styles.flexCenter,
          { flexWrap: "wrap" },
        ]}
      >
        {slotComponents}
      </View>
      <View style={[Styles.mt16, Styles.ph16]}>
        <View>
          <CheckBox
            title={t("Auto-use pouch items")}
            checked={state.global.autoUsePouch}
            onPress={autoUsePouchToggle}
          />
        </View>
      </View>
    </View>
  );
}
