import React, {
  useCallback,
  useEffect,
  useMemo,
  useRef,
  useState,
} from "react";
import type { ViewStyle } from "react-native";
import { Platform, StyleSheet, View } from "react-native";
import {
  ButtonProps,
  Button as PaperButton,
  useTheme,
} from "react-native-paper";
import { valuesOf } from "../../utils/CoreUtils";
// import PaperButton from "./PaperButton";
import { useAuxOptions } from "./AuxOptionsContext";
import { Text } from "./Text";

export function Button(
  props: Omit<Omit<ButtonProps, "onPress">, "children"> & {
    onPress: () => void;
    enableAutoRepress?: boolean;
    autoRepressConfig?: {
      minTimeMs?: number;
      startingThreshold?: number;
      startingRepressTime?: number;
      dropoffMultiplier?: number;
    };
    ButtonComponent?: React.ElementType;
    size?: number;
    TextComponent?: React.ElementType;
    children?: React.ReactNode;
    numberOfLines?: number | null;
    passthroughStyle?: ViewStyle;
    disableDisabledRenderingOptimization?: boolean;
  },
) {
  const {
    disabled,
    autoRepressConfig,
    enableAutoRepress,
    style,
    labelStyle,
    passthroughStyle,
    disableDisabledRenderingOptimization,
    TextComponent,
    ...rest
  } = props;
  const pressed = useRef(false);
  const disabledRef = useRef(disabled);
  disabledRef.current = disabled;
  const currentRepressTimeout = useRef(300);
  const currentRepressHandler = useRef<NodeJS.Timeout | null>(null);
  const [flash, setFlash] = useState(false);
  const shouldDisableRenderingOptimization =
    disableDisabledRenderingOptimization || rest.ButtonComponent != null;

  useEffect(() => {
    if (flash) {
      const timeoutHandler = setTimeout(
        () => setFlash(false),
        (autoRepressConfig?.minTimeMs ?? 100) / 3,
      );
      return () => clearTimeout(timeoutHandler);
    }
  }, [flash]);
  const rePress = useCallback(() => {
    if (pressed.current) {
      if (!disabledRef.current) {
        props.onPress && props.onPress();
      }
      setFlash(true);
      currentRepressTimeout.current = Math.max(
        autoRepressConfig?.minTimeMs ?? 100,
        currentRepressTimeout.current *
          (autoRepressConfig?.dropoffMultiplier ?? 0.94),
      );
      currentRepressHandler.current = setTimeout(
        rePress,
        currentRepressTimeout.current,
      );
    }
  }, [props.onPress]);
  const onPressInCallback = useCallback(() => {
    pressed.current = true;
    currentRepressHandler.current = setTimeout(() => {
      if (pressed.current && !disabledRef.current) {
        currentRepressTimeout.current =
          autoRepressConfig?.startingRepressTime ?? 300;
        rePress();
      }
    }, autoRepressConfig?.startingThreshold ?? 600);
  }, [rePress]);
  const onPressOutCallback = useCallback(() => {
    pressed.current = false;
    if (currentRepressHandler.current != null) {
      clearTimeout(currentRepressHandler.current);
      currentRepressHandler.current = null;
    }
  }, [rePress]);
  useEffect(() => {
    return () => {
      onPressOutCallback();
    };
  }, [onPressOutCallback]);
  const ButtonComponent = props.ButtonComponent ?? PaperButton;
  const theme = useTheme();
  const auxOptions = useAuxOptions();
  const basicLabelStyle =
    props.compact && Platform.OS != "web"
      ? theme.fonts.labelSmall
      : theme.fonts.labelLarge;
  const verticallyNarrowLayout = auxOptions.compactLayout;
  const verticalLabelStyle = verticallyNarrowLayout
    ? styles.compactLabel
    : undefined;
  const styleFlash = useMemo(
    () => [passthroughStyle, flash && { opacity: 0.9 }],
    [passthroughStyle, flash],
  );
  const computedLabelStyle = useMemo(
    () => [basicLabelStyle, verticalLabelStyle, labelStyle],
    [basicLabelStyle, verticalLabelStyle, labelStyle],
  );
  const activeButton = useMemo(
    () => (
      <ButtonComponent
        onPressIn={enableAutoRepress ? onPressInCallback : undefined}
        onPressOut={enableAutoRepress ? onPressOutCallback : undefined}
        disabled={shouldDisableRenderingOptimization ? disabled : false}
        mode="contained"
        style={styleFlash}
        labelStyle={computedLabelStyle}
        TextComponent={TextComponent ?? Text}
        {...rest}
      />
    ),
    [
      enableAutoRepress,
      onPressInCallback,
      onPressOutCallback,
      styleFlash,
      computedLabelStyle,
      TextComponent,
      shouldDisableRenderingOptimization ? disabled : false,
      ...valuesOf(rest),
    ],
  );
  const disabledButton = useMemo(
    () =>
      shouldDisableRenderingOptimization ? null : (
        <ButtonComponent
          onPressIn={enableAutoRepress ? onPressInCallback : undefined}
          onPressOut={enableAutoRepress ? onPressOutCallback : undefined}
          disabled={true}
          mode="contained"
          style={styleFlash}
          labelStyle={computedLabelStyle}
          TextComponent={TextComponent ?? Text}
          {...rest}
        />
      ),
    [
      enableAutoRepress,
      onPressInCallback,
      onPressOutCallback,
      styleFlash,
      computedLabelStyle,
      TextComponent,
      shouldDisableRenderingOptimization,
      ...valuesOf(rest),
    ],
  );
  return useMemo(
    () =>
      shouldDisableRenderingOptimization ? (
        <View style={style}>
          <View>{activeButton}</View>
        </View>
      ) : (
        <View style={style}>
          <View style={disabled && { display: "none" }}>{activeButton}</View>
          <View style={!disabled && { display: "none" }}>{disabledButton}</View>
        </View>
      ),
    [
      style,
      disabled,
      activeButton,
      disabledButton,
      shouldDisableRenderingOptimization,
    ],
  );
}

const styles = StyleSheet.create({
  compactLabel: {
    marginVertical: 5,
  },
});
