import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { ActionEffect, DoActionArgs } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { CombatStat } from "../../exploration/CombatStats";
import { TemporaryEffect } from "../../temporaryeffects/TemporaryEffect";
import {
  TemporaryEffectData,
  TemporaryEffects,
  grantTemporaryEffect,
} from "../../temporaryeffects/TemporaryEffects";
import { TransformationType } from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";

class BlurTemporaryEffect extends TemporaryEffect {
  getId() {
    return "blur";
  }

  getDisplayName(): string {
    return t("Blur");
  }

  getIcon() {
    return require("./blur.png");
  }

  isCommonBuffEnabled(): { stat: CombatStat; type: TransformationType }[] {
    return [{ stat: CombatStat.Dodge, type: TransformationType.Addition }];
  }

  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("+{{amount}}:dodge:", {
      amount: formatNumber(temporaryEffectData.params.commonBuff.Dodge),
    });
  }
}

export const blurTemporaryEffect = new BlurTemporaryEffect();
TemporaryEffects.register(blurTemporaryEffect);

class Blur extends CombatSpellBase {
  getId(): string {
    return "blur";
  }

  getSpellName(): string {
    return t("Blur");
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.COMBAT_BUFF;
  }

  getBaseManaCost(state: GameState): number {
    return 1200;
  }

  canUseOutsideOfCombat(): boolean {
    return true;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 30,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        unit: ":dodge:",
      },
      duration: {
        value: 15.0,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    const effects = this.getActionEffects(state);
    return grantTemporaryEffect(blurTemporaryEffect.getId(), effects.duration, {
      params: { commonBuff: { Dodge: effects.magnitude } },
    })(state);
  }

  getDisplayDescription(state: GameState): string {
    return t("Make yourself harder to spot and target by enemies.");
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    return t(
      "^+{{amount}}^<{{amountExplanation}}>:dodge: for ^{{duration}}^<{{durationExplanation}}> sec",
      {
        amount: formatNumber(effects.magnitude),
        amountExplanation: explanations.magnitude,
        duration: formatNumber(effects.duration),
        durationExplanation: explanations.duration,
      },
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Air: 14 };
  }
}

export const blur = new Blur();
