import { t } from "../../utils/I18n";
import { GameState } from "../GameState";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { cursed } from "./Cursed";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  clearTemporaryEffect,
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class StatusImmunityBase extends TemporaryEffect {
  constructor(effect: TemporaryEffect) {
    super();
    this.effect = effect;
  }
  effect: TemporaryEffect;
  getId(): string {
    return "statusImmunity" + this.effect.getId();
  }
  getDisplayName(): string {
    return t("Protection from {{status}}", {
      status: this.effect.getDisplayName(),
    });
  }
  isBeneficial(): boolean {
    return true;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Immunity to {{status}}", {
      status: this.effect.getDisplayName(),
    });
  }
}

// Status immunity only works for these temporary effects
const ALLOWED_STATUS_IMMUNITY_EFFECTS = [cursed];

export function specificStatusImmunityTemporaryEffect(
  effect: TemporaryEffect,
): StatusImmunityBase {
  return TemporaryEffects.getById(
    "statusImmunity" + effect.getId(),
  ) as StatusImmunityBase;
}

export function loadSpecificStatusImmunity() {
  for (let effect of ALLOWED_STATUS_IMMUNITY_EFFECTS) {
    const statusImmunity = new StatusImmunityBase(effect);
    TemporaryEffects.register(statusImmunity);
    registerTemporaryEffectListener(
      "statusImmunityDebuffPrevention" + effect.getId(),
      (state, temporaryEffect) => {
        if (
          !hasTemporaryEffect(
            state,
            statusImmunity.getId(),
            temporaryEffect.target,
          ) ||
          temporaryEffect.id != effect.getId()
        ) {
          return temporaryEffect;
        }
        // Prevent!
        temporaryEffect.endTime = getSecondsPlayed(state) - 1.0;
        return temporaryEffect;
      },
    );
    // On grant, dispel the effect
    registerTemporaryEffectListener(
      "statusImmunityDispelling" + effect.getId(),
      (state, temporaryEffect) => {
        if (
          temporaryEffect.id == statusImmunity.getId() &&
          temporaryEffect.endTime > getSecondsPlayed(state)
        ) {
          state = clearTemporaryEffect(
            effect.getId(),
            temporaryEffect.target,
          )(state);
        }
        return temporaryEffect;
      },
    );
  }
}
