import { t } from "../../utils/I18n";
import { BattlerStats } from "../exploration/enemies/Enemy";
import Slime from "../exploration/enemies/training/Slime";
import { GameState } from "../GameState";
import { FamiliarClass } from "./FamiliarClass";

class SlimeFamiliar extends FamiliarClass {
  getId(): string {
    return "slimeFamiliar";
  }
  getPicture(state: GameState) {
    return Slime.getPicture(state);
  }
  getName(): string {
    return t("Slime");
  }
  getBaseStats(): BattlerStats {
    return {
      maxHP: 50,
      attack: 15,
      defense: 5,
      attackDelay: 3.2,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }
  getPerLevelStatMultiplier(): BattlerStats {
    return {
      maxHP: 1.06,
      attack: 1.06,
      defense: 1.06,
      attackDelay: 1,
      accuracy: 1,
      dodge: 1,
      crit: 1,
    };
  }
}

export const slimeFamiliar = new SlimeFamiliar();
