import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { ActionEffect, DoActionArgs } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { cloakOfFlames } from "../../temporaryeffects/CloakOfFlames";
import { grantTemporaryEffect } from "../../temporaryeffects/TemporaryEffects";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";

class CloakOfFlames extends CombatSpellBase {
  getId(): string {
    return "cloakOfFlames";
  }

  getSpellName(): string {
    return t("Cloak of Flames");
  }

  getElement(): SpellElement {
    return SpellElement.Fire;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.COMBAT_BUFF;
  }

  getBaseManaCost(state: GameState): number {
    return 2000;
  }

  canUseOutsideOfCombat(): boolean {
    return true;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 1.0,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        unit: t("x"),
      },
      duration: {
        value: 15.0,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    const effects = this.getActionEffects(state);
    return grantTemporaryEffect(cloakOfFlames.getId(), effects.duration, {
      params: { magnitude: effects.magnitude },
    })(state);
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Surround yourself with fiery flames that retaliate when you are hurt.",
    );
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    return t(
      "Reflects back ^{{multiplier}}x^<{{multiplierExplanation}}> of the damage received for ^{{duration}}^<{{durationExplanation}}> sec",
      {
        multiplier: formatNumber(effects.magnitude, { showDecimals: true }),
        multiplierExplanation: explanations.magnitude,
        duration: formatNumber(effects.duration),
        durationExplanation: explanations.duration,
      },
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Fire: 24 };
  }
}

export const cloakOfFlamesSpell = new CloakOfFlames();
