import { cacheByTickCount } from "../Caching";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import {
  applyTransformationsPermaCached,
  clearTransformationCache,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Building } from "./Building";

export type BuildingData = {
  current: number;
  turnedOn: number;
};

const Buildings = new DataStore<Building>();

export { Buildings };

const STARTING_LAND = 150;

const landCapTags = [TransformationTags.Land, TransformationTags.Cap];
export function getTotalLand(state: GameState): number {
  return Math.floor(
    applyTransformationsPermaCached(landCapTags, state, STARTING_LAND),
  );
}

export function clearLandCache(state: GameState): GameState {
  return clearTransformationCache(new Set(landCapTags), state);
}

export function getUsedLand(state: GameState): number {
  return cacheByTickCount(state, "usedLand", (state) => {
    let totalLand = 0;
    Buildings.getAll().forEach(
      (building) =>
        (totalLand +=
          building.getLandRequired(state) * getBuildingAmount(state, building)),
    );
    return totalLand;
  });
}

export function getFreeLand(state: GameState): number {
  let totalLand = getTotalLand(state);
  totalLand -= getUsedLand(state);
  return Math.max(totalLand, 0);
}

export function getBuildingAmount(
  state: GameState,
  building: Building,
): number {
  return state.run.buildings?.[building.getId()]?.current || 0;
}

export function getBuildingAmountTurnedOn(
  state: GameState,
  building: Building,
): number {
  return state.run.buildings?.[building.getId()]?.turnedOn || 0;
}

export function changeBuildingsTurnedOn(
  building: Building,
  amount: number,
  state: GameState,
): GameState {
  const id = building.getId();

  if (state.run.buildings?.[id] == null) {
    state.run.buildings[id] = {
      current: 0,
      turnedOn: 0,
    };
  }

  const newAmount = getBuildingAmountTurnedOn(state, building) + amount;
  if (newAmount < 0 || newAmount > getBuildingAmount(state, building)) {
    return state;
  }
  state.run.buildings[id].turnedOn = newAmount;

  BuildingAmountListeners.getAll().forEach(
    (transform) => (state = transform(state, building)),
  );

  return state;
}

export const BuildingAmountListeners: DataStore<
  (state: GameState, building: Building) => GameState
> = new DataStore();
