import { valuesOf } from "../../../../utils/CoreUtils";
import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import {
  hasEverHadResource,
  Resource,
  translateResource,
} from "../../../Resources";
import {
  AutocastConditionParameterDisplay,
  AutocastConditionParameterOption,
  AutocastConditionParameterType,
} from "../../AutocastConditionParameterType";

export class ResourceAutocastConditionParameterType extends AutocastConditionParameterType {
  getDisplay(): AutocastConditionParameterDisplay {
    return AutocastConditionParameterDisplay.PICKER;
  }

  getOptions(state: GameState): AutocastConditionParameterOption[] {
    return valuesOf(Resource)
      .filter((resource) => hasEverHadResource(state, resource))
      .map((resource) => ({
        label: translateResource(resource),
        value: resource,
      }));
  }

  validate(state: GameState, value: string): string | undefined {
    if (!valuesOf(Resource).includes(value as Resource)) {
      return t("You must select a resource");
    }
    return;
  }
}
