import { formatMultiplier } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { ActionEffect } from "../../../action/Action";
import { AttackTarget } from "../../../exploration/AttackTarget";
import {
  CombatStat,
  clearCombatStatCache,
} from "../../../exploration/CombatStats";
import { SpellElement } from "../../../spells/Elements";
import {
  TransformationType,
  registerTransformation,
} from "../../../transformation/Transformation";
import { TransformationValueType } from "../../../transformation/TransformationTags";
import { SynchroChangeListeners } from "../../Synchro";
import { SynchroBonuses } from "../../SynchroBonuses";
import { EarthSynchroBonus } from "../BaseSynchroBonuses";

class EarthWaterSynchroBonus extends EarthSynchroBonus {
  getElement2(): SpellElement {
    return SpellElement.Water;
  }

  protected getBaseBonusEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 0.04,
        valueType: TransformationValueType.Percent,
      },
    };
  }

  getEffect(state: GameState, amount: number): string {
    const magnitude = this.getBonusEffect(state, "magnitude", amount);
    const explanation = this.getBonusEffectExplanations(
      state,
      amount,
      "magnitude",
    )["magnitude"];
    return t("^{{magnitude}}^<{{explanation}}> Max:hp:", {
      magnitude: formatMultiplier(magnitude),
      explanation,
    });
  }
}
export function loadEarthWaterSynchroBonus() {
  const bonus = new EarthWaterSynchroBonus();
  SynchroBonuses.register(bonus);
  registerTransformation(
    [[AttackTarget.Player, CombatStat.MaxHP]],
    bonus.getId(),
    bonus.getName(),
    TransformationType.Multiplier,
    (state) => {
      return bonus.getCurrentBonusEffect(state, "magnitude");
    },
  );
  SynchroChangeListeners.register((state) => {
    state = clearCombatStatCache(state, AttackTarget.Player, CombatStat.MaxHP);
    return state;
  }, "earthWaterCombatStatCacheClearer");
}
