import { t } from "../../../../utils/I18n";
import { jumpEventToMessageIfExists } from "../../../events/Events";
import { GameState } from "../../../GameState";
import { StoneskinTemporaryEffect } from "../../../spells/earth/Stoneskin";
import { StrengthTemporaryEffect } from "../../../spells/fire/Strength";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import {
  getCurrentEnemyHPFraction,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class KnightInShiningArmor extends Enemy {
  getId(): string {
    return "knightInShiningArmor";
  }

  getName(): string {
    return t("Knight in Shining Armor");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 1000,
      attack: 25,
      defense: 20,
      attackDelay: 3.3,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./knightInShiningArmor.png");
  }

  getLevel(): number {
    return 5;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 100;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 1;
  }

  getNextAction(state: GameState): BattlerAction {
    if (
      !hasTemporaryEffect(
        state,
        StoneskinTemporaryEffect.getId(),
        AttackTarget.Enemy,
      ) &&
      getCurrentEnemyHPFraction(state) > 0.5 &&
      Math.random() < 0.5
    ) {
      return {
        name: t("Stoneskin"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: grantTemporaryEffect(StoneskinTemporaryEffect.getId(), 30, {
          target: AttackTarget.Enemy,
          params: { commonBuff: { DefenseMultiplier: 1.75 } },
        }),
      };
    }
    if (
      Math.random() < 0.5 &&
      getCurrentEnemyHPFraction(state) < 0.5 &&
      !hasTemporaryEffect(
        state,
        StrengthTemporaryEffect.getId(),
        AttackTarget.Enemy,
      )
    ) {
      return {
        name: t("Strength"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: (state) =>
          grantTemporaryEffect(StrengthTemporaryEffect.getId(), 30, {
            target: AttackTarget.Enemy,
            params: { commonBuff: { AttackMultiplier: 1.25 } },
          })(state),
      };
    }

    if (Math.random() < 0.2 && getCurrentEnemyHPFraction(state) < 0.3) {
      return {
        name: t("Triple Attack"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.7);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.7);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.7);
          return state;
        },
      };
    }

    if (Math.random() < 0.3) {
      return {
        name: t("Double Attack"),
        delay: this.getAttackDelay(state) * 1.1,
        transform: (state) => {
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.8);
          state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.8);
          return state;
        },
      };
    }

    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  isBoss(): boolean {
    return true;
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = jumpEventToMessageIfExists(state, "howToFight", "afterFight");
    return state;
  }

  getTips(): string {
    return t(
      "Wow... so gallant! I wish he had come for me instead! In any case... he likes to attack multiple times in a row, but the attacks are weaker than a normal attack. High :defense: armor is pretty effective, but make sure you also have a way to heal, like with Health Potions - you might need Water Lv6 for that. It's obvious he is going easy on you!",
    );
  }
}

export default new KnightInShiningArmor();
