import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import {
  EquipmentSlot,
  getMaxEquippedItemsPerSlot,
  loadEquipmentLoadoutFromPosition,
} from "../../backend/items/Equipment";
import { t } from "../../utils/I18n";
import { Button } from "../utility/Button";
import { GameIcon } from "../utility/icons/GameIcon";
import { useGameState } from "../utility/useGameState";

export function EquipmentSetButtons() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const setNumber = state.run.lastEquipmentLoadoutModified + 1;

  const equipmentSetButtonCallback = useCallback((index: number) => {
    stateRef.current.apply((state) =>
      loadEquipmentLoadoutFromPosition(state, index),
    );
  }, []);

  const theme = useTheme();

  const equipmentSetButtons = useMemo(() => {
    return [0, 1, 2, 3].map((i) => {
      const equipmentSet = state.run.equipmentLoadouts?.[i];
      const icons: React.ReactNode[] = [];
      for (let slot of [
        EquipmentSlot.Hand,
        EquipmentSlot.Body,
        EquipmentSlot.Accessory,
      ]) {
        const capacity = getMaxEquippedItemsPerSlot(state, slot);
        for (let i = 0; i < capacity; i++) {
          const itemOccurrence = equipmentSet?.equipment?.[slot]?.[i];
          if (itemOccurrence != null) {
            const icon = (
              <GameIcon
                key={slot + i}
                icon={itemOccurrence.itemId}
                small={true}
              />
            );
            icons.push(icon);
          } else {
            icons.push(<View key={slot + i} style={styles.emptyIcon} />);
          }
        }
      }

      return (
        <View key={i} style={styles.buttonContainer}>
          <Button
            style={styles.setButton}
            contentStyle={{ margin: 0, flex: 1 }}
            disabled={setNumber == i + 1}
            compact={true}
            onPress={equipmentSetButtonCallback.bind(null, i)}
          >
            {t(`Set {{number}}`, { number: i + 1 })}
          </Button>
          <View style={styles.iconsContainer}>{icons}</View>
        </View>
      );
    });
  }, [
    JSON.stringify(state.run.equipmentLoadouts),
    equipmentSetButtonCallback,
    setNumber,
    theme,
  ]);

  return (
    <View style={styles.equipmentSetButtonsRow}>{equipmentSetButtons}</View>
  );
}

const styles = StyleSheet.create({
  equipmentSetButtonsRow: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    flexWrap: "wrap",
  },
  setButton: {
    width: 70,
  },
  setText: {
    marginRight: 8,
  },
  smallButtonTitle: {
    fontSize: 12,
  },
  iconsContainer: {
    flexDirection: "row",
    alignItems: "center",
    justifyContent: "center",
    height: 16,
    width: 48,
    marginTop: 2,
  },
  emptyIcon: {
    width: 16,
  },
  buttonContainer: {
    marginLeft: 4,
    alignItems: "center",
    width: 70,
  },
});
