import { transmutePaper } from "./transmute/materials/Paper";
import { transmuteThickHide } from "./transmute/materials/ThickHide";
import { registerTransmutationSpellAndItem } from "./transmute/TransmutationSpell";

export function loadItemsDemo() {
  loadPouch();

  // Loot
  Items.register(Wildgrass);
  Items.register(SlimeDrop);
  Items.register(Cherry);
  Items.register(VenomPowder);
  Items.register(EtherealEelExtract);

  // Transmutable
  loadElementalShards();
  registerTransmutationSpellAndItem(transmutePaper);
  registerTransmutationSpellAndItem(transmuteCopperOre);
  registerTransmutationSpellAndItem(transmuteCopperIngot);
  registerTransmutationSpellAndItem(transmuteCotton);
  loadElementalPrisms();

  // Fire
  registerTransmutationSpellAndItem(TransmuteHandAxe);
  registerTransmutationSpellAndItem(transmuteLeatherArmor);
  loadPotionOfMuscle();
  registerTransmutationSpellAndItem(TransmuteBattleAxe);
  registerTransmutationSpellAndItem(transmuteCopperTomahawk);
  registerTransmutationSpellAndItem(transmuteCopperWarhammer);
  registerTransmutationSpellAndItem(transmuteCollarOfPower);
  registerTransmutationSpellAndItem(transmuteReinforcedLeatherArmor);
  registerTransmutationSpellAndItem(transmuteRedConflagration);
  registerTransmutationSpellAndItem(transmuteWandOfFire);

  // Earth
  registerTransmutationSpellAndItem(TransmuteLongsword);
  registerTransmutationSpellAndItem(TransmuteStoneMail);
  registerTransmutationSpellAndItem(transmuteSmallShield);
  loadPotionOfToughness();
  registerTransmutationSpellAndItem(transmuteBastardSword);
  registerTransmutationSpellAndItem(transmuteCopperRapier);
  registerTransmutationSpellAndItem(transmuteCopperArmor);
  registerTransmutationSpellAndItem(transmuteCopperShield);
  registerTransmutationSpellAndItem(transmuteStoneEgg);
  registerTransmutationSpellAndItem(transmuteTurtleArmor);
  registerTransmutationSpellAndItem(transmuteThickHide);

  // Water
  registerTransmutationSpellAndItem(TransmuteManaTear);
  registerTransmutationSpellAndItem(TransmuteRod);
  registerTransmutationSpellAndItem(TransmuteWizardsRobe);
  registerTransmutationSpellAndItem(TransmuteHealthPotion);
  loadPotionOfVitality();
  registerTransmutationSpellAndItem(transmuteWandOfWater);
  registerTransmutationSpellAndItem(transmuteElderWizardsRobe);
  registerTransmutationSpellAndItem(transmuteWizardsHat);
  registerTransmutationSpellAndItem(transmuteTunicOfGoodHealth);
  registerTransmutationSpellAndItem(transmuteRingOfRegeneration);

  // Air
  registerTransmutationSpellAndItem(TransmuteFeatheredHat);
  registerTransmutationSpellAndItem(TransmuteKnuckles);
  registerTransmutationSpellAndItem(TransmuteBreezyShirt);
  loadPotionOfAlacrity();
  registerTransmutationSpellAndItem(transmuteGlaive);
  registerTransmutationSpellAndItem(transmuteCloakOfDarkness);
  registerTransmutationSpellAndItem(transmuteCopperGauntlet);
  registerTransmutationSpellAndItem(transmuteEvasiveGarb);
  registerTransmutationSpellAndItem(transmuteGaleScepter);

  // Poison
  registerTransmutationSpellAndItem(transmuteCharmOfPoisonResistance);
  registerTransmutationSpellAndItem(transmuteDagger);
  registerTransmutationSpellAndItem(transmuteAntidotePotion);
  registerTransmutationSpellAndItem(transmuteVenomousBrew);
  registerTransmutationSpellAndItem(transmuteAssassinsBaselard);
  registerTransmutationSpellAndItem(transmuteAssassinsCloak);
  registerTransmutationSpellAndItem(transmuteSoulStone);
  loadPotionOfEvasion();
  registerTransmutationSpellAndItem(transmuteWandOfPoison);
  registerTransmutationSpellAndItem(transmuteShadowBlade);
}
