import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { getBuildingAmount, getUsedLand } from "../buildings/Buildings";
import { essenceCatalyst } from "../buildings/catalysts/EssenceCatalyst";
import { ManaSpout } from "../buildings/mana/ManaSpout";
import { ManaShard } from "../buildings/storages/ManaShard";
import { isDungeonFloorCompletedThisRun } from "../exploration/dungeons/Dungeons";
import SewerEntrance from "../exploration/dungeons/sewers/SewerEntrance";
import SewerHazardousSector from "../exploration/dungeons/sewers/SewerHazardousSector";
import SewerHideout from "../exploration/dungeons/sewers/SewerHideout";
import { FLOOR_FOR_ELEMENT } from "../exploration/dungeons/training/TrainingTowers";
import {
  getSuccessfulExplorationsForFloor,
  isExplorationUnlocked,
} from "../exploration/Exploration";
import { Flag } from "../Flag";
import { hasFlag } from "../Flags";
import { getResourceAmount, getResourceCap, Resource } from "../Resources";
import { RESOURCE_FOR_ELEMENT } from "../spells/channeling/ChannelingResources";
import {
  getElementExp,
  getPrimaryElementMaxLevelForRun,
  getTotalExpRequiredForLevel,
  SpellElement,
} from "../spells/Elements";
import { clearAllHints, pushHint } from "./Hints";

export function loadHintsDemo() {
  clearAllHints();
  // 1
  pushHint({
    description: t("Gather more Mana"),
    isCompleted: (state) => hasFlag(state, Flag.ChannelingUnlocked),
    progress: (state) => getResourceAmount(state, Resource.Mana) / 10.0,
  });
  pushHint({
    description: t("Channel Water and Air Essence"),
    isCompleted: (state) => hasFlag(state, Flag.ManaSpoutsUnlocked),
  });
  pushHint({
    description: t("Build a Mana Spout"),
    isCompleted: (state) =>
      getBuildingAmount(state, ManaSpout) > 0 ||
      hasFlag(state, Flag.ManaShardsUnlocked),
  });
  pushHint({
    description: t("Channel Earth Essence"),
    isCompleted: (state) => hasFlag(state, Flag.ManaShardsUnlocked),
    progress: (state) => getResourceAmount(state, Resource.EarthEssence) / 30.0,
  });
  pushHint({
    description: t("Build a Mana Shard"),
    isCompleted: (state) =>
      getBuildingAmount(state, ManaShard) > 0 ||
      hasFlag(state, Flag.StoragesUnlocked),
  });
  pushHint({
    description: t("Fill up your Essence storage"),
    isCompleted: (state) => hasFlag(state, Flag.StoragesUnlocked),
    progress: (state) =>
      Math.max(
        ...valuesOf(RESOURCE_FOR_ELEMENT).map((resource) =>
          getResourceAmount(state, resource),
        ),
      ) / 100.0,
  });
  pushHint({
    description: t("Build an Essence Storage"),
    isCompleted: (state) => getResourceCap(state, Resource.FireEssence) >= 200,
  });
  pushHint({
    description: t("Channel Fire Essence"),
    isCompleted: (state) => hasFlag(state, Flag.CatalystsUnlocked),
    progress: (state) => getResourceAmount(state, Resource.FireEssence) / 110.0,
  });
  pushHint({
    description: t("Build an Essence Catalyst"),
    isCompleted: (state) =>
      getBuildingAmount(state, essenceCatalyst) > 0 ||
      hasFlag(state, Flag.StudyUnlocked),
  });
  pushHint({
    description: t(
      "Study your primary element and level it up to Lv2, while continuing to build your home",
    ),
    isCompleted: (state) => getPrimaryElementMaxLevelForRun(state) >= 2,
    progress: (state) =>
      getElementExp(state, state.run.primaryElement ?? SpellElement.Fire) /
      getTotalExpRequiredForLevel(
        state,
        2,
        state.run.primaryElement ?? SpellElement.Fire,
      ),
  });
  pushHint({
    description: t("Transmute items, sell them and accumulate Coins"),
    isCompleted: (state) => hasFlag(state, Flag.WizardsUnlocked),
    progress: (state) => getResourceAmount(state, Resource.Coins) / 500.0,
  });
  pushHint({
    description: t(
      "Continue transmuting and selling items and accumulate at least 1000 Coins",
    ),
    isCompleted: (state) => hasFlag(state, Flag.SaleStallsUnlocked),
    progress: (state) => getResourceAmount(state, Resource.Coins) / 1000.0,
  });
  pushHint({
    description: t(
      "Continue transmuting and selling items and accumulate at least 1500 Coins",
    ),
    isCompleted: (state) => hasFlag(state, Flag.ResearchersUnlocked),
    progress: (state) => getResourceAmount(state, Resource.Coins) / 1500.0,
  });
  pushHint({
    description: t("Continue building furniture and using up land"),
    isCompleted: (state) => hasFlag(state, Flag.LandPurchaseUnlocked),
    progress: (state) => getUsedLand(state) / 120.0,
  });
  pushHint({
    description: t("Level up your primary element to Lv6"),
    isCompleted: (state) => isExplorationUnlocked(state),
    progress: (state) =>
      getElementExp(state, state.run.primaryElement ?? SpellElement.Fire) /
      getTotalExpRequiredForLevel(
        state,
        6,
        state.run.primaryElement ?? SpellElement.Fire,
      ),
  });
  pushHint({
    description: t("Explore the Tower of Training of your primary element"),
    isCompleted: (state) => {
      const floor = FLOOR_FOR_ELEMENT[state.run.primaryElement];
      return floor == null || floor.canFightBoss(state);
    },
    progress: (state) => {
      const floor = FLOOR_FOR_ELEMENT[state.run.primaryElement];
      if (floor == null) {
        return 1.0;
      }
      return (
        1.0 -
        (floor.getExplorationSuccessesForBoss(state) -
          getSuccessfulExplorationsForFloor(state, floor.getId())) /
          floor.getExplorationSuccessesForBoss(state)
      );
    },
  });
  pushHint({
    description: t(
      "Defeat the boss of the Tower of Training of your primary element",
    ),
    isCompleted: (state) => {
      if (hasFlag(state, Flag.EnhancementsUnlocked)) {
        return true;
      }
      const floor = FLOOR_FOR_ELEMENT[state.run.primaryElement];
      return floor == null || isDungeonFloorCompletedThisRun(state, floor);
    },
  });
  pushHint({
    description: t(
      "Explore and defeat the boss of all of the Towers of Training",
    ),
    isCompleted: (state) => {
      if (hasFlag(state, Flag.EnhancementsUnlocked)) {
        return true;
      }
      for (let floor of valuesOf(FLOOR_FOR_ELEMENT)) {
        if (!isDungeonFloorCompletedThisRun(state, floor)) {
          return false;
        }
      }
      return true;
    },
  });
  pushHint({
    description: t(
      'Complete the "Final Exam" event by defeating the Head Instructor',
    ),
    isCompleted: (state) => hasFlag(state, Flag.EnhancementsUnlocked),
  });
  pushHint({
    description: t("Explore the Sewer Entrance"),
    isCompleted: (state) => SewerEntrance.canFightBoss(state),
  });
  pushHint({
    description: t("Defeat the boss of the Sewer Entrance"),
    isCompleted: (state) =>
      isDungeonFloorCompletedThisRun(state, SewerEntrance),
  });
  pushHint({
    description: t("Explore the Hazardous Sector"),
    isCompleted: (state) => SewerHazardousSector.canFightBoss(state),
  });
  pushHint({
    description: t("Defeat the boss of the Hazardous Sector"),
    isCompleted: (state) =>
      isDungeonFloorCompletedThisRun(state, SewerHazardousSector),
  });
  pushHint({
    description: t("Explore the Sewer Hideout"),
    isCompleted: (state) => SewerHideout.canFightBoss(state),
  });
  pushHint({
    description: t("Defeat the boss of the Sewer Hideout"),
    isCompleted: (state) => hasFlag(state, Flag.VastDesertUnlocked),
  });
  pushHint({
    description: t("Enjoy your time in the World"),
    isCompleted: (state) => false,
  });
}
