import { Schematics } from "../events/storyline/cavern/SchematicsStoryline";
import { Flag } from "../Flag";
import { hasEverHadFlag, hasFlag } from "../Flags";
import { GameState } from "../GameState";
import { DataStore } from "../generic/DataStore";
import { Item } from "./Item";

const Items = new DataStore<Item>();

export { Items };

export function isInventoryUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.TransmutationUnlocked);
}

export function isInventoryEverUnlocked(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.TransmutationUnlocked);
}

export function isCraftingUnlocked(state: GameState): boolean {
  return hasFlag(state, Flag.TransmutationUnlocked);
}

export function isItemQualityUnlocked(state: GameState): boolean {
  return Schematics.isBonusActive(state);
}

export function hasEverUnlockedCrafting(state: GameState): boolean {
  return hasEverHadFlag(state, Flag.TransmutationUnlocked);
}

export function combineAll(state: GameState): GameState {
  let combined;
  do {
    combined = false;
    const inventory = state.run.inventory;
    for (let stack of inventory) {
      const item = Items.getById(stack.itemOccurrence.itemId);
      const combineActions = item.getCombineActions(
        state,
        stack.itemOccurrence.params,
      );
      if (combineActions.length > 0) {
        const combineAllAction = combineActions[combineActions.length - 1];
        if (combineAllAction.isEnabled) {
          state = combineAllAction.transform(state);
          combined = true;
          break;
        }
      }
    }
  } while (combined);
  return state;
}

export function sellLowQualityItems(state: GameState): GameState {
  const inventory = state.run.inventory;
  let sold = false;
  do {
    sold = false;
    for (let stack of inventory) {
      const item = Items.getById(stack.itemOccurrence.itemId);
      const hasHigherQuality =
        state.run.inventory.filter(
          (itemStack) =>
            itemStack.itemOccurrence.itemId == item.getId() &&
            (itemStack.itemOccurrence.params?.itemQuality ?? 0) >
              (stack.itemOccurrence.params?.itemQuality ?? 0),
        ).length > 0;

      if (hasHigherQuality) {
        const saleActions = item.getSaleActions(
          state,
          stack.itemOccurrence.params,
        );
        const saleAction = saleActions[saleActions.length - 1];
        if (saleAction?.isEnabled) {
          state = saleAction.transform(state);
          sold = true;
          break;
        }
      }
    }
  } while (sold);
  return state;
}
