import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { modifyTargetCurrentHP } from "../exploration/Exploration";
import {
  CombatActionResult,
  CombatActionResultListeners,
  pushCombatActionResult,
} from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import { SpellElement } from "../spells/Elements";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectData,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class CloakOfFlames extends TemporaryEffect {
  getId(): string {
    return "cloakOfFlames";
  }
  getDisplayName(): string {
    return t("Cloak of Flames");
  }
  isBeneficial(): boolean {
    return true;
  }
  getIcon() {
    return require("./cloakOfFlames.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "Reflects back {{multiplier}}x of the damage received as Fire damage",
      {
        multiplier: formatNumber(temporaryEffectData.params.magnitude, {
          showDecimals: true,
        }),
      },
    );
  }
}

export const cloakOfFlames = new CloakOfFlames();

export function loadCloakOfFlames() {
  TemporaryEffects.register(cloakOfFlames);

  CombatActionResultListeners.register((state, result) => {
    if (!hasTemporaryEffect(state, cloakOfFlames.getId(), result.target)) {
      return state;
    }

    if (
      !result.isHit ||
      result.damage <= 0 ||
      result.tags.includes(TransformationTags.Reflected) ||
      result.source == result.target
    ) {
      return state;
    }

    const data = getTemporaryEffectData(
      state,
      cloakOfFlames.getId(),
      result.target,
    );
    const magnitude = data.params.magnitude;

    const damageReflected = result.damage * magnitude;
    state = modifyTargetCurrentHP(
      state,
      result.source,
      -damageReflected,
      cloakOfFlames.getDisplayName(),
    );

    const actionResult: CombatActionResult = {
      id: Math.random().toString(),
      damage: damageReflected,
      isHit: true,
      isCrit: false,
      source: result.target,
      time: getSecondsPlayed(state),
      target: result.source,
      tags: [SpellElement.Fire, TransformationTags.Reflected],
    };
    state = pushCombatActionResult(state, actionResult);

    return state;
  });
}
