import { EvilIcons, MaterialIcons } from "@expo/vector-icons";
import moment from "moment";
import React, { useCallback, useMemo, useRef } from "react";
import { FlatList, StyleSheet, View } from "react-native";
import { List, useTheme } from "react-native-paper";
import { INITIAL_STATE } from "../../backend/GameState";
import {
  EventOccurrenceData,
  addOccurrenceToQueue,
  getEventMessageById,
} from "../../backend/events/Events";
import { clone } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { useAuxOptions } from "../utility/AuxOptionsContext";
import CategoryContainer from "../utility/CategoryContainer";
import Divider from "../utility/Divider";
import Styles from "../utility/Styles";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";

export const INITIAL_STATE_CLONED = clone(INITIAL_STATE);

function EventListItem(props: {
  item: EventOccurrenceData;
  onPress: () => void;
}) {
  if (!props.item.currentMessageId) {
    return null;
  }
  const currentMessageData = getEventMessageById(props.item.currentMessageId);
  if (!currentMessageData) {
    return null;
  }
  const title = currentMessageData.getTitle(props.item?.params);
  const state = useGameState();
  const description = useMemo(
    () =>
      moment(props.item.timestamp).format("h:mm A") +
      " - " +
      currentMessageData.getDescription(
        INITIAL_STATE_CLONED,
        props.item?.params,
      ),
    [props.item.timestamp],
  );
  const theme = useTheme();
  const auxOptions = useAuxOptions();
  return useMemo(() => {
    return (
      <View>
        <List.Item
          onPress={props.onPress}
          accessible={true}
          accessibilityLabel={title}
          accessibilityRole="button"
          title={title}
          description={description}
          descriptionNumberOfLines={1}
          style={auxOptions.compactLayout && { paddingVertical: 0 }}
          left={() =>
            currentMessageData.isActionable(state, props.item?.params) && (
              <View style={[Styles.ml16, Styles.flexCenter]}>
                <EvilIcons
                  name="exclamation"
                  type="evilicon"
                  size={24}
                  style={{ height: 24 }}
                  color={theme.colors.onBackground}
                />
              </View>
            )
          }
          right={() => (
            <List.Icon
              icon={currentMessageData.isTerminal() ? "check" : "chevron-right"}
            />
          )}
        />
        <Divider />
      </View>
    );
  }, [
    title,
    description,
    currentMessageData.isTerminal(),
    currentMessageData.isActionable(state, props.item?.params),
    theme,
    auxOptions.compactLayout,
  ]);
}

const EventListItemMemoized = React.memo(
  EventListItem,
  (prevProps, nextProps) => {
    return prevProps.item.currentMessageId === nextProps.item.currentMessageId;
  },
);

export function EventList(props: { style?: any }) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const keyExtractor = useCallback(
    (item: EventOccurrenceData, index: number) => item.eventOccurrenceId,
    [],
  );

  const renderItem = useCallback((props: any) => {
    const onPress = () => {
      stateRef.current.apply(
        addOccurrenceToQueue(props.item.eventOccurrenceId),
      );
    };
    return <EventListItemMemoized {...props} onPress={onPress} />;
  }, []);

  const sortedEvents = Object.values(state.run.eventOccurrences).sort(
    (a, b) => b.timestamp - a.timestamp,
  );

  const stringifiedEvents = useMemo(
    () => JSON.stringify(state.run.eventOccurrences),
    [sortedEvents],
  );

  const terminalEvents = useMemo(
    () =>
      sortedEvents
        .filter((event) => {
          return (
            event.currentMessageId &&
            getEventMessageById(event.currentMessageId).isTerminal()
          );
        })
        .slice(0, 20),
    [stringifiedEvents],
  );
  const ongoingEvents = useMemo(
    () =>
      sortedEvents.filter((event) => {
        return (
          event.currentMessageId &&
          !getEventMessageById(event.currentMessageId).isTerminal()
        );
      }),
    [stringifiedEvents],
  );

  const eventCategories = useMemo(() => {
    const eventCategories: { title: string; data: EventOccurrenceData[] }[] =
      [];
    if (ongoingEvents.length > 0) {
      eventCategories.push({ title: t("Ongoing"), data: ongoingEvents });
    }
    if (terminalEvents.length > 0) {
      eventCategories.push({ title: t("Finished"), data: terminalEvents });
    }
    return eventCategories;
  }, [ongoingEvents, terminalEvents]);

  const categoryKeyExtractor = useCallback(
    (item: { title: string; data: EventOccurrenceData[] }, index: number) =>
      item.title,
    [],
  );

  const categoryRenderItem = useCallback(
    (props: any) => {
      return (
        <CategoryContainer
          titleStyle={styles.categoryTitle}
          title={props.item.title}
        >
          <FlatList
            keyExtractor={keyExtractor}
            data={props.item.data}
            renderItem={renderItem}
          />
        </CategoryContainer>
      );
    },
    [keyExtractor, renderItem],
  );

  const theme = useTheme();

  return useMemo(
    () => (
      <View
        style={[
          { backgroundColor: theme.colors.background },
          Styles.flex,
          props.style,
        ]}
      >
        <View
          accessible={true}
          accessibilityLabel={t("Events")}
          accessibilityRole="header"
          style={styles.emptyTitleContainer}
        >
          <MaterialIcons
            size={24}
            name="notifications"
            style={styles.titleIcon}
            color={theme.colors.onBackground}
          />
          <Text h2>{t("Events")}</Text>
        </View>
        <FlatList
          keyExtractor={categoryKeyExtractor}
          data={eventCategories}
          renderItem={categoryRenderItem}
          style={Styles.flex}
        />
      </View>
    ),
    [theme, categoryKeyExtractor, categoryRenderItem, eventCategories],
  );
}

const styles = StyleSheet.create({
  categoryTitle: {
    marginLeft: 8,
  },
  emptyTitleContainer: {
    flexDirection: "row",
    justifyContent: "center",
    marginTop: 8,
  },
  titleIcon: {
    paddingRight: 8,
    height: 28,
  },
  eventTitleContainer: {
    flexDirection: "row",
    alignItems: "center",
  },
  titleText: {
    flex: 1,
  },
  actionableIcon: {
    marginRight: 4,
  },
});
