import memoize from "sonic-memoize";
import { valuesOf } from "../../../utils/CoreUtils";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { cacheByArbitraryVersion } from "../../Caching";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import { Resource, clearResourceCapCache } from "../../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { SpellElement, getUnlockedElements } from "../../spells/Elements";
import { registerSpell } from "../../spells/Spells";
import { RESOURCE_FOR_ELEMENT } from "../../spells/channeling/ChannelingResources";
import {
  TransformationType,
  applyTransformationsCached,
  explainTransformationsText,
  registerTransformation,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import {
  BuildingAmountListeners,
  Buildings,
  getBuildingAmount,
} from "../Buildings";

class EssenceStorage extends Building {
  getId(): string {
    return "essenceStorage";
  }

  getName(): string {
    return t("Essence Storage");
  }

  perBuildingCapTags = memoize((resource) => [
    TransformationTags.PerBuildingCap,
    resource,
    this.getId(),
  ]);

  perBuildingCapParams = memoize((resource) => ({ resource }));

  getPerStorageBonusCap(state: GameState, resource: Resource): number {
    return applyTransformationsCached(
      this.perBuildingCapTags(resource),
      state,
      400,
      this.perBuildingCapParams(resource),
    );
  }

  perBuildingCapExplanationParams = memoize((resource) => ({
    resource,
    unit: t(" Max{{resourceIcon}}", {
      resourceIcon: `:${resource.toLowerCase()}:`,
    }),
  }));

  getPerStorageBonusCapExplanation(
    state: GameState,
    resource: Resource,
  ): string {
    return explainTransformationsText(
      this.perBuildingCapTags(resource),
      state,
      400,
      this.perBuildingCapExplanationParams(resource),
    );
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "A magical storage that allows you to accumulate more elemental Essence.",
    );
  }

  _getDisplayEffectUncached(state: GameState): string {
    return getUnlockedElements(state)
      .map((element) => {
        return t("^+{{amount}}^<{{explanation}}> Max{{resourceIcon}}", {
          amount: formatNumber(
            this.getPerStorageBonusCap(state, RESOURCE_FOR_ELEMENT[element]),
          ),
          resourceIcon: `:${RESOURCE_FOR_ELEMENT[element].toLowerCase()}:`,
          explanation: this.getPerStorageBonusCapExplanation(
            state,
            RESOURCE_FOR_ELEMENT[element],
          ),
        });
      })
      .join(t(", "));
  }

  getDisplayEffect(state: GameState): string {
    return cacheByArbitraryVersion(
      state,
      "essenceStoragesEffect",
      (state) => this._getDisplayEffectUncached(state),
      (state) =>
        JSON.stringify(
          getUnlockedElements(state).map((element) => {
            return this.getPerStorageBonusCap(
              state,
              RESOURCE_FOR_ELEMENT[element],
            );
          }),
        ),
    );
  }

  getBaseLandRequired(): number {
    return 5;
  }

  canTurnOff(): boolean {
    return false;
  }
}

const essenceStorage = new EssenceStorage();

class BuildEssenceStorage extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.STORAGE] };
  }

  getBuilding(): EssenceStorage {
    return essenceStorage;
  }

  shouldNeverHaveDemolishBlockers(): boolean {
    return false;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return { EarthEssence: 60 };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      EarthEssence: 1.4,
    };
  }

  getBaseManaCost(state: GameState): number {
    return 20;
  }

  isVisible(state: GameState): boolean {
    return hasFlag(state, Flag.StoragesUnlocked) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Earth;
  }
}

const buildEssenceStorage = new BuildEssenceStorage();

export function loadEssenceStorages() {
  Buildings.register(essenceStorage);
  registerSpell(buildEssenceStorage);

  for (let element of valuesOf(SpellElement)) {
    registerTransformation(
      [[TransformationTags.Cap, RESOURCE_FOR_ELEMENT[element]]],
      "essenceStorage" + element,
      t("Essence Storage"),
      TransformationType.Addition,
      (state, params) => {
        return (
          getBuildingAmount(state, essenceStorage) *
          essenceStorage.getPerStorageBonusCap(
            state,
            RESOURCE_FOR_ELEMENT[element],
          )
        );
      },
    );
  }

  BuildingAmountListeners.register((state, building) => {
    if (building == essenceStorage) {
      state = clearResourceCapCache(state);
    }
    return state;
  }, essenceStorage.getId());
}

export { buildEssenceStorage, essenceStorage };
