import memoize from "sonic-memoize";
import { valuesOfCached } from "../utils/CoreUtils";
import { t } from "../utils/I18n";
import { hasWorldFlag, setWorldFlag } from "./Flags";
import { GameState, GameStateTransform } from "./GameState";
import { addGameStateListener } from "./GameStateListeners";
import {
  applyTransformations,
  explainTransformationsText,
} from "./transformation/Transformation";
import { TransformationTags } from "./transformation/TransformationTags";

type ResourceInfo = {
  baseCap: number;
  icon: string;
};

export enum Resource {
  Mana = "Mana",
  FireEssence = "FireEssence",
  EarthEssence = "EarthEssence",
  WaterEssence = "WaterEssence",
  AirEssence = "AirEssence",
  PoisonEssence = "PoisonEssence",
  MindEssence = "MindEssence",
  LifeEssence = "LifeEssence",
  ElectricEssence = "ElectricEssence",
  DeathEssence = "DeathEssence",
  HolyEssence = "HolyEssence",
  TimeEssence = "TimeEssence",
  SpaceEssence = "SpaceEssence",
  Coins = "Coins",
  Monstium = "Monstium",
  TimePiece = "TimePiece",
}

export function translateResource(resource: Resource): string {
  switch (resource) {
    case Resource.Mana:
      return t("Mana");
    case Resource.FireEssence:
      return t("Fire");
    case Resource.EarthEssence:
      return t("Earth");
    case Resource.WaterEssence:
      return t("Water");
    case Resource.AirEssence:
      return t("Air");
    case Resource.PoisonEssence:
      return t("Poison");
    case Resource.MindEssence:
      return t("Mind");
    case Resource.LifeEssence:
      return t("Life");
    case Resource.ElectricEssence:
      return t("Electric");
    case Resource.DeathEssence:
      return t("Death");
    case Resource.HolyEssence:
      return t("Holy");
    case Resource.TimeEssence:
      return t("Time");
    case Resource.SpaceEssence:
      return t("Space");
    case Resource.Coins:
      return t("Coins");
    case Resource.Monstium:
      return t("Monstium");
    case Resource.TimePiece:
      return t("Time Pieces");

    // Do not add a default
  }
}

