export function getResourceInfo(resource: Resource): ResourceInfo {
  return RESOURCES[resource];
}

export function getSortedResourceKeys(state: GameState) {
  return valuesOfCached(Resource).filter(
    (resource) => state.run.resources[resource] != null,
  );
}

const resourceCapTags = memoize((resourceName) => [
  resourceName,
  TransformationTags.Cap,
]);

const resourceCapParams = memoize((resourceName) => ({
  resource: resourceName,
}));

function calculateResourceCap(
  state: GameState,
  resourceName: Resource,
): number {
  let value = state.temp.resourceCapCache[resourceName];
  if (value == null) {
    value = applyTransformations(
      resourceCapTags(resourceName),
      state,
      getResourceInfo(resourceName).baseCap,
      resourceCapParams(resourceName),
    );
    state.temp.resourceCapCache[resourceName] = value;
  }
  return value;
}

export function registerResourceCapClearer(
  id: string,
  valueFn: (state: GameState) => any,
) {
  addGameStateListener(
    id + "-resourceCapClearer",
    (_state) => (state) => {
      state.temp.resourceCapCache = {};
      return state;
    },
    valueFn,
  );
}

export function clearResourceCapCache(state: GameState): GameState {
  state.temp.resourceCapCache = {};
  return state;
}

export function explainResourceCap(
  state: GameState,
  resourceName: Resource,
): string {
  return explainTransformationsText(
    resourceCapTags(resourceName),
    state,
    getResourceInfo(resourceName).baseCap,
    { resource: resourceName },
  );
}

export function getResourceCap(
  state: GameState,
  resourceName: Resource,
): number {
  return calculateResourceCap(state, resourceName);
}

export function getResourceAmount(state: GameState, resource: Resource) {
  return state.run.resources?.[resource] || 0;
}

export function isResourceCapped(state: GameState, resource: Resource) {
  return getResourceAmount(state, resource) >= getResourceCap(state, resource);
}

export function hasEverHadResource(state: GameState, resource: Resource) {
  return hasWorldFlag(state, "hasEverHadResource-" + resource);
}

function grantResourceImpl(name: Resource, amount: number, state: GameState) {
  if (state.run.resources?.[name] == null) {
    state.run.resources[name] = 0;
  }
  let resource = state.run.resources[name] as number;
  if (amount < 0) {
    resource += amount;
  } else {
    const cap = getResourceCap(state, name);
    const overCap = resource > cap;

    if (!overCap) {
      resource += amount;
      resource = Math.min(resource, cap);
    }
    state = setWorldFlag("hasEverHadResource-" + name)(state);
  }
  resource = Math.max(resource, 0);
  state.run.resources[name] = resource;
  return state;
}

export function grantResource(
  this: any,
  name: Resource,
  amount: number,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return grantResourceImpl.bind(this, name, amount);
}

