import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import { poison } from "../../../temporaryeffects/Poison";
import { TemporaryEffect } from "../../../temporaryeffects/TemporaryEffect";
import {
  clearTemporaryEffect,
  grantTemporaryEffect,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "../../../temporaryeffects/TemporaryEffects";
import { getSecondsPlayed } from "../../../timetick/TotalTimePlayed";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  calculateAttackTarget,
  drainingEnemyAttackEffect,
  getCurrentEnemyHPFraction,
  modifyEnemyCurrentHP,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { pushCombatActionResult } from "../../ExplorationActionResult";
import { Enemies, getCurrentEnemy } from "../Enemies";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class DoubleTroubleTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "doubleTrouble";
  }
  getDisplayName(): string {
    return t("Double Trouble");
  }
  isDispellable(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Transforms into a different form");
  }
}

class BotanicalSwarmTemporaryEffect extends TemporaryEffect {
  getId(): string {
    return "botanicalSwarm";
  }
  getDisplayName(): string {
    return t("Botanical Swarm");
  }
  isDispellable(): boolean {
    return false;
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Transforms into a different form");
  }
}

const doubleTroubleTemporaryEffect = new DoubleTroubleTemporaryEffect();
const botanicalSwarmTemporaryEffect = new BotanicalSwarmTemporaryEffect();

class ManEatingPlant extends Enemy {
  getId(): string {
    return "manEatingPlant";
  }

  getName(): string {
    return t("Man-Eating Plant");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 8000,
      attack: 60,
      defense: 50,
      attackDelay: 2.8,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPhase(state: GameState): number {
    if (getCurrentEnemy(state) != this) {
      return 1;
    }
    if (
      hasTemporaryEffect(
        state,
        botanicalSwarmTemporaryEffect.getId(),
        AttackTarget.Enemy,
      )
    ) {
      return 3;
    }
    if (
      hasTemporaryEffect(
        state,
        doubleTroubleTemporaryEffect.getId(),
        AttackTarget.Enemy,
      )
    ) {
      return 2;
    }
    return 1;
  }

  getPicture(state: GameState) {
    switch (this.getPhase(state)) {
      case 3:
        return require("./manEatingPlant3.png");
      case 2:
        return require("./manEatingPlant2.png");
      default:
        return require("./manEatingPlant.png");
    }
  }

  getLevel(): number {
    return 20;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 500;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 200;
  }

  getNextActionPhase3(state: GameState): BattlerAction {
    if (Math.random() < 0.4) {
      return {
        name: t("Triple Absorb"),
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state: GameState) => {
          state = drainingEnemyAttackEffect(state, this.getAttack(state) * 0.7);
          state = drainingEnemyAttackEffect(state, this.getAttack(state) * 0.7);
          state = drainingEnemyAttackEffect(state, this.getAttack(state) * 0.7);
          return state;
        },
      };
    }

    return {
      name: t("Triple Attack"),
      delay: this.getAttackDelay(state) * 1.2,
      transform: (state) => {
        state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.7);
        state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.7);
        state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.7);
        return state;
      },
    };
  }

  getNextActionPhase2(state: GameState): BattlerAction {
    const target = calculateAttackTarget(state);
    if (
      !hasTemporaryEffect(state, poison.getId(), target) &&
      Math.random() < 0.1
    ) {
      return {
        name: t("Slow-Acting Poison"),
        delay: this.getAttackDelay(state) * 1.4,
        transform: (state) => {
          const target = calculateAttackTarget(state);
          state = grantTemporaryEffect(poison.getId(), 60, {
            target,
            params: {
              magnitude: this.getAttack(state) / 20,
            },
          })(state);
          return state;
        },
      };
    }

    if (Math.random() < 0.4) {
      return {
        name: t("Double Absorb"),
        delay: this.getAttackDelay(state) * 1.3,
        transform: (state: GameState) => {
          state = drainingEnemyAttackEffect(state, this.getAttack(state) * 0.8);
          state = drainingEnemyAttackEffect(state, this.getAttack(state) * 0.8);
          return state;
        },
      };
    }

    return {
      name: t("Double Attack"),
      delay: this.getAttackDelay(state) * 1.1,
      transform: (state) => {
        state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.8);
        state = standardEnemyAttackEffect(state, this.getAttack(state) * 0.8);
        return state;
      },
    };
  }

  getNextAction(state: GameState): BattlerAction {
    const phase = this.getPhase(state);
    if (getCurrentEnemyHPFraction(state) < 0.2 && phase < 3) {
      // Switch to phase 3
      return {
        name: t("Mutate"),
        delay: this.getAttackDelay(state) * 2.0,
        transform: (state) => {
          state = grantTemporaryEffect(
            botanicalSwarmTemporaryEffect.getId(),
            600,
            {
              target: AttackTarget.Enemy,
            },
          )(state);
          state = clearTemporaryEffect(
            doubleTroubleTemporaryEffect.getId(),
            AttackTarget.Enemy,
          )(state);
          const healAmt = this.getMaxHP(state) * 0.2;
          state = modifyEnemyCurrentHP(state, healAmt);
          const actionResult = {
            id: Math.random().toString(),
            damage: -healAmt,
            isCrit: false,
            isHit: true,
            time: getSecondsPlayed(state),
            source: AttackTarget.Enemy,
            target: AttackTarget.Enemy,
            tags: [],
          };
          state = pushCombatActionResult(state, actionResult);
          return state;
        },
      };
    } else if (getCurrentEnemyHPFraction(state) < 0.6 && phase < 2) {
      // Switch to phase 2
      return {
        name: t("Mutate"),
        delay: this.getAttackDelay(state) * 1.5,
        transform: (state) => {
          state = grantTemporaryEffect(
            doubleTroubleTemporaryEffect.getId(),
            600,
            {
              target: AttackTarget.Enemy,
            },
          )(state);
          state = clearTemporaryEffect(
            botanicalSwarmTemporaryEffect.getId(),
            AttackTarget.Enemy,
          )(state);
          return state;
        },
      };
    }

    if (phase == 3) {
      return this.getNextActionPhase3(state);
    } else if (phase == 2) {
      return this.getNextActionPhase2(state);
    }

    const target = calculateAttackTarget(state);

    if (
      !hasTemporaryEffect(state, poison.getId(), target) &&
      Math.random() < 0.2
    ) {
      return {
        name: t("Deadly Poison"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: (state) => {
          const target = calculateAttackTarget(state);
          state = grantTemporaryEffect(poison.getId(), 12, {
            target,
            params: {
              magnitude: this.getAttack(state) / 6,
            },
          })(state);
          return state;
        },
      };
    }

    if (Math.random() < 0.4) {
      return {
        name: t("Absorb"),
        delay: this.getAttackDelay(state) * 1.1,
        transform: (state: GameState) => {
          state = drainingEnemyAttackEffect(state, this.getAttack(state));
          return state;
        },
      };
    }

    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  isBoss(): boolean {
    return true;
  }

  getBackdropColor(): string {
    return "#84a";
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "sewerHideout");
    return state;
  }

  getTips(): string {
    return t(
      "This giant plant is unbelievably strong. It might be the hardest fight we have done so far. Very strong attacks, very strong poison. Using Antidote (needs Poison Lv7) can certainly help us here, too, as we can neutralize the very strong poison it has. But the worst part is that it mutates into even more dangerous forms eventually! I wonder if higher :defense: would help us on the later forms...",
    );
  }
}

export const manEatingPlant = new ManEatingPlant();

export function loadManEatingPlant() {
  TemporaryEffects.register(doubleTroubleTemporaryEffect);
  TemporaryEffects.register(botanicalSwarmTemporaryEffect);
  Enemies.register(manEatingPlant);
}
