import { t } from "../../../utils/I18n";
import { isDungeonFloorCompletedThisRun } from "../../exploration/dungeons/Dungeons";
import TrainingTowerAir from "../../exploration/dungeons/training/TrainingTowerAir";
import TrainingTowerEarth from "../../exploration/dungeons/training/TrainingTowerEarth";
import TrainingTowerFire from "../../exploration/dungeons/training/TrainingTowerFire";
import { FLOOR_FOR_ELEMENT } from "../../exploration/dungeons/training/TrainingTowers";
import TrainingTowerWater from "../../exploration/dungeons/training/TrainingTowerWater";
import HeadInstructor from "../../exploration/enemies/training/HeadInstructor";
import {
  addEnemyDeathListener,
  startCombat,
} from "../../exploration/Exploration";
import { Flag } from "../../Flag";
import { hasFlag, setFlag } from "../../Flags";
import { TransmuteHealthPotion } from "../../items/equipment/pouch/HealthPotion";
import { SpellCastListeners } from "../../spells/SpellCastListeners";
import { addUICommand } from "../../UICommands";
import { buildEvent } from "../EventMessageBuilder";
import { triggerEvent } from "../Events";
import { EventTag } from "../GameEvent";

export function loadTrainingTowerProgression() {
  const firstBossUnlockedEventBuilder = buildEvent(
    "firstBossUnlocker",
    t("One More Test"),
    [EventTag.MAIN_QUEST],
  );

  firstBossUnlockedEventBuilder.message(
    t(`You have made a lot of progress fighting enemies left and right in this combat training. You feel quite strong!
    
You encounter a door, more ornately decorated than the rest. It appears you are at the top of the tower. A sign reads to the side: *"/enemyName/"*. You sense this is probably some sort of final test for the tower, and ready up for a tough battle...

**You can now fight the boss of /dungeonFloorName/! You can do so by pressing the "Fight Boss" button in the Exploration screen.**"`),
  );
  const firstBossUnlockedEvent = firstBossUnlockedEventBuilder.build();

  addEnemyDeathListener(
    firstBossUnlockedEvent.getId() + "Triggerer",
    (state) => {
      if (!state.run.primaryElement) {
        return state;
      }
      const floor = FLOOR_FOR_ELEMENT[state.run.primaryElement];

      if (
        !firstBossUnlockedEvent.hasTriggered(state) &&
        floor?.canFightBoss?.(state) &&
        !isDungeonFloorCompletedThisRun(state, floor)
      ) {
        return triggerEvent(firstBossUnlockedEvent, {
          dungeonFloorName: floor.getFloorName(),
          enemyName: floor.getBoss(state).getName(),
        })(state);
      }
      return state;
    },
  );

  const restOfTrainingTowersUnlockerEventBuilder = buildEvent(
    "restOfTrainingTowersUnlocker",
    t("Training is Not Over"),
    [EventTag.MAIN_QUEST],
  ).setOnTrigger(setFlag(Flag.RestOfTrainingUnlocked));

  restOfTrainingTowersUnlockerEventBuilder.message(
    t(`You fell the enemy after what felt like a longer battle than the others. It felt slightly challenging. More than that, however, you feel excited and exhilarated - you seem to enjoy combat much more than you thought you would!
  
It appears that is not enough. You find a paper in this last room, which mentions that there are other training towers, meant for those who specialize in the other elements - but a true Wizard should be able to triumph over all of them, it says. You decide to take it up to the challenge - you could use some more difficult combat...

**You have unlocked three more Training Towers in Exploration!**`),
  );

  const restOfTrainingTowersUnlockerEvent =
    restOfTrainingTowersUnlockerEventBuilder.build();

  addEnemyDeathListener(
    restOfTrainingTowersUnlockerEvent.getId() + "Triggerer",
    (state) => {
      const floor = FLOOR_FOR_ELEMENT[state.run.primaryElement];
      if (floor == null) {
        return state;
      }
      if (
        !restOfTrainingTowersUnlockerEvent.hasTriggered(state) &&
        !hasFlag(state, Flag.RestOfTrainingUnlocked) &&
        isDungeonFloorCompletedThisRun(state, floor)
      ) {
        return triggerEvent(restOfTrainingTowersUnlockerEvent)(state);
      }
      return state;
    },
  );

  const finalTrainingEventBuilder = buildEvent(
    "finalTraining",
    t("Final Exam"),
    [EventTag.MAIN_QUEST],
  );

  finalTrainingEventBuilder.message(
    t(
      `As you defeat the strongest enemy of the last Training Tower, you hear clapping from behind you. You turn around, and you see a stylish woman, dressed in white robes and holding a staff with her arm against her body. She appears to be a capable wizard. She is the one clapping at you.`,
    ),
  );
  finalTrainingEventBuilder.message(
    t(`"Congratulations on your victory", she exclaims. "You've certainly come a long way, haven't you?"
    
"Let me introduce myself", she continues. "I am the head instructor of combat - my name is Giselle. I am the one who extended you the invitation for this extra training course at these four Towers of Training. I don't think you have noticed me, but I saw you at the top of each of the Towers - and noticed your fights against the four guardians at the top. You did an excellent job." You thank her for the kind words.`),
  );
  finalTrainingEventBuilder.message(
    t(`"You have yet one more challenge to go through before you can complete the training, however", she says. "Each of those Towers tested your ability and affinity with a different Element. But, in actual combat against real adversaries, a Wizard will need to know the strengths and the weakness of each element, and combine them in order to craft the right strategy. It is a very important lesson."
    
Then she tells you more: "Because of that reason, I will see how you do in battle by myself, in a duel. If you are able to hold your own against me, I will deem you worthy of having finished the training."`),
  );
  finalTrainingEventBuilder
    .message(
      t(`You shudder a little. She looks quite strong, a lot stronger than anything you've fought so far.
    
Regardless, it really does seem like this is the final test in this special course. You ready yourself for combat against the powerful foe.`),
    )
    .option(t("Fight the Head Instructor"), {
      transform: (state) => {
        state = addUICommand(state, "goToExploration");
        state = addUICommand(state, "closeAllMenus");
        state = addUICommand(state, "closeEventOverlay");
        state = startCombat(HeadInstructor)(state);
        return state;
      },
    })
    .option(t("Maybe later"))
    .end();

  finalTrainingEventBuilder
    .message(
      t(`Another Fireball? You strike at the Head Instructor before she has the chance to finish her spell. She flinches! She asks you to stop. Then she looks at you - she looks quite visibly tired - and nods.
      
"*You pass*", she exclaims. "Impressive performance. You have definitely understood how to combine all the elements together and become a strong all-around combatant." You thank her.`),
    )
    .tag("afterFight");

  finalTrainingEventBuilder.message(
    t(`"With what you've learned in this special training, you should now be ready for some of the challenges in the wide world outside", she says. "I am hoping the experience you got has increased your confidence." She is right. You feel a lot stronger.
        
"I will leave you with one last thing, however - have you ever wondered what to do with the :monstium:Monstium that you have been finding from all those enemies?"

The question stumps you. You have, indeed, wondered what was the purpose, and you have been keeping much of it...`),
  );

  finalTrainingEventBuilder
    .message(
      t(`"Monstium is a key material used for *Enhancements*, medium-to-long-term all-encompassing magical effects that radically affect so many different things", she explains. "For example, there are Enhancements for greatly increasing your Essence storage, giving you extra combat power, enabling you to build new types of furniture, and many more."
     
She also tells you about Paper, another important requirement for Enhancements. Thankfully, Paper can apparently be transmuted quite easily - she also tells you how to do so.

She tells you about a few Enhancements and what you need in order to activate them. You listen intently, while realizing how deeply transformative those Enhancements could be. You want to take notes, but you did not bring such material with you - but you think you can remember, at least this time.`),
    )
    .option(
      t("Continue"),
      { transform: setFlag(Flag.EnhancementsUnlocked) },
      "end",
    )
    .end();

  finalTrainingEventBuilder
    .message(
      t(`The instructor finally finishes her narration. You thank her - you are certain this will come in useful for the future. And then, finally, you head back home - you can't wait to apply those Enhancements, and head beyond the boundaries of your house, to see what you can learn... You are not sure if this gets you any closer, but you feel renewed enthusiasm about your quest to obtain the Philosopher's Stone.
    
**You have unlocked the ability to transmute Paper!**

**You have unlocked Enhancements! Enhancements use :monstium:Monstium, as well as other items, to improve many aspects of the game. The effects do *not* persist through Retirement. You can find these on the Enhance screen!**`),
    )
    .tag("end");

  const finalTrainingEvent = finalTrainingEventBuilder.build();

  addEnemyDeathListener(finalTrainingEvent.getId() + "Triggerer", (state) => {
    if (
      !hasFlag(state, Flag.EnhancementsUnlocked) &&
      !finalTrainingEvent.hasTriggered(state) &&
      [
        TrainingTowerFire,
        TrainingTowerEarth,
        TrainingTowerWater,
        TrainingTowerAir,
      ].every((floor) => isDungeonFloorCompletedThisRun(state, floor))
    ) {
      return triggerEvent(finalTrainingEvent)(state);
    }
    return state;
  });

  const potionCraftedEventBuilder = buildEvent(
    "potionFirstCrafted",
    t("Pouch Items"),
    [],
  );

  potionCraftedEventBuilder.message(
    t(`After gathering many materials, you finally managed to follow the recipe to Transmute your first :healthPotion:Health Potion!
    
You quickly check out the reference on it, searching through your notes. How were you meant to use it...?`),
  );
  potionCraftedEventBuilder.message(
    t(`Ah, you finally found the right page! There's *two* large messages. You read the first one: *"Potions are Pouch Items. **Pouch Items are not consumable, and they will not disappear when used.** They are considered pieces of equipment."*
      
That sounds... convenient. So you can drink it *and it will never run out*? You continue reading the second one: *"Pouch items can **only be used during combat**, and each one will have **a certain amount of uses per combat**. For example, :healthPotion:Health Potions have 3 uses per combat. **Entering a new combat will refresh the pouch items and restore their uses.**"*

You think about what you've just read... These potions sound extremely useful. You're eager to equip it - right away.

*Tip: You can review this information or read more about Pouch Items in the :help:Help screen!*`),
  );
  const potionCraftedEvent = potionCraftedEventBuilder.build();

  SpellCastListeners.register((state, spell) => {
    if (
      spell == TransmuteHealthPotion &&
      !potionCraftedEvent.hasEverTriggered(state)
    ) {
      state = triggerEvent(potionCraftedEvent)(state);
    }
    return state;
  });
}
