import { clone, valuesOfCached } from "../utils/CoreUtils";
import { t } from "../utils/I18n";
import { CombatClassesStoryline } from "./classes/CombatClassesStoryline";
import { hasEverTriggeredEnding } from "./ending/Endings";
import { convenienceStoryline } from "./events/storyline/desert/Convenience";
import { FURNITURE_PRESERVATION_OPTIONS } from "./events/storyline/sewers/FurniturePreservation";
import { permacastStoryline } from "./events/storyline/sewers/PermaCast";
import { TicTacToeStoryline } from "./events/storyline/sewers/TicTacToeStoryline";
import { TomeOfToxicKnowledge } from "./events/storyline/sewers/TomeOfToxicKnowledgeStoryline";
import { AttackTarget } from "./exploration/AttackTarget";
import { CombatStat } from "./exploration/CombatStats";
import { GameState, RUN_INITIAL_STATE, WORLD_INITIAL_STATE } from "./GameState";
import { Resource } from "./Resources";
import { retire } from "./Retire";
import { SpellElement } from "./spells/Elements";
import { Storyline } from "./storylines/Storyline";
import { Storylines } from "./storylines/Storylines";
import {
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "./transformation/Transformation";
import { TransformationTags } from "./transformation/TransformationTags";

function newGamePlusReset(state: GameState): GameState {
  state = retire(state, false);

  state.world.totalTimesResetted = 0;
  return state;
}

export const MAX_DIFFICULTY_LEVEL = 11;
export const MAX_BOON_LEVEL = 10;

export function getMaxPossibleDifficultyLevel(state: GameState): number {
  return Math.min(
    state.global.newGamePlusMax +
      (hasEverTriggeredEnding(state, "ending_1") &&
      (state.world.worldDifficultyLevel >= 10 ||
        state.global.newGamePlusMax < 10)
        ? 1
        : 0),
    MAX_DIFFICULTY_LEVEL,
  );
}

export const BASE_BOON_POINTS_PER_WORLD = 15;
const BOON_POINTS_TAGS = [TransformationTags.BoonPointsEarned];

export function getTotalAvailableBoonPoints(state: GameState): number {
  return getNewBoonPointsAvailable(state) + state.global.totalBoonPointsEarned;
}

export function getNewBoonPointsAvailable(state: GameState): number {
  return applyTransformations(
    BOON_POINTS_TAGS,
    state,
    BASE_BOON_POINTS_PER_WORLD,
  );
}

export function triggerNewGamePlus(
  state: GameState,
  sheerPowerLevel: number,
  storylineOracleLevel: number,
  blazingSpeedLevel: number,
  worldDifficultyLevel: number,
): GameState {
  // First, validate the parameters
  if (
    worldDifficultyLevel > getMaxPossibleDifficultyLevel(state) ||
    getTotalAvailableBoonPoints(state) <
      getPointsRequiredForConfiguration(
        sheerPowerLevel,
        storylineOracleLevel,
        blazingSpeedLevel,
      ) ||
    sheerPowerLevel > MAX_BOON_LEVEL ||
    storylineOracleLevel > MAX_BOON_LEVEL ||
    blazingSpeedLevel > MAX_BOON_LEVEL
  ) {
    // Do nothing
    return state;
  }

  state.global.newGamePlusCount++;
  state.global.newGamePlusMax = getMaxPossibleDifficultyLevel(state);
  state.global.totalBoonPointsEarned += getNewBoonPointsAvailable(state);
  state.world = clone(WORLD_INITIAL_STATE);
  state.world.startTimestamp = Date.now();
  state.world.sheerPowerLevel = sheerPowerLevel;
  state.world.storylineOracleLevel = storylineOracleLevel;
  state.world.blazingSpeedLevel = blazingSpeedLevel;
  state.world.worldDifficultyLevel = worldDifficultyLevel;
  state.run = clone(RUN_INITIAL_STATE);

  // Must apply all the Storylines for the Oracle
  let storylinesToGrant: Storyline[] = [];
  if (storylineOracleLevel >= 10) {
    const storylinesToAvoid = getStorylinesToAvoidForOracle10();
    storylinesToGrant = Storylines.getAll().filter(
      (storyline) => !storylinesToAvoid.includes(storyline),
    );
  } else {
    let oracleLevel = storylineOracleLevel;
    while (oracleLevel > 0) {
      storylinesToGrant = storylinesToGrant.concat(
        getStorylinesToUnlockForSpecificOracleLevel(oracleLevel),
      );
      oracleLevel--;
    }
  }
  for (let storyline of storylinesToGrant) {
    state = storyline.complete(state);
    state = storyline.activateBonus(state);
  }

  state = newGamePlusReset(state);
  return state;
}

function fibonacci(n: number) {
  if (n <= 1) {
    return 1;
  }
  return fibonacci(n - 1) + fibonacci(n - 2);
}

export function getIncrementalPointsRequiredForLevel(level: number): number {
  return fibonacci(3 + level);
}

export function getTotalPointsRequiredForLevel(level: number): number {
  let points = 0;
  for (let l = 1; l <= level; l++) {
    points += getIncrementalPointsRequiredForLevel(l);
  }
  return points;
}

export function getPointsRequiredForConfiguration(
  sheerPowerLevel: number,
  storylineOracleLevel: number,
  blazingSpeedLevel: number,
): number {
  return (
    getTotalPointsRequiredForLevel(sheerPowerLevel) +
    getTotalPointsRequiredForLevel(storylineOracleLevel) +
    getTotalPointsRequiredForLevel(blazingSpeedLevel)
  );
}

export function getNewGamePlusCount(state: GameState): number {
  return state.global.newGamePlusCount;
}

export function getSheerPowerLevel(state: GameState): number {
  return state.world.sheerPowerLevel;
}

export function getStorylineOracleLevel(state: GameState): number {
  return state.world.storylineOracleLevel;
}

export function getBlazingSpeedLevel(state: GameState): number {
  return state.world.blazingSpeedLevel;
}

export function getWorldDifficultyLevel(state: GameState): number {
  return state.world.worldDifficultyLevel;
}

export function getNewGamePlusElementMaxLevelBonus(
  sheerPowerLevel: number,
): number {
  if (sheerPowerLevel < 8) {
    return Math.ceil(sheerPowerLevel / 2.0) * 10;
  }
  return Math.floor(sheerPowerLevel / 2.0) * 10;
}

export function getNewGamePlusFamiliarMaxLevelBonus(
  sheerPowerLevel: number,
): number {
  return sheerPowerLevel * 4;
}

export function getNewGamePlusItemQualityMaxBonus(
  sheerPowerLevel: number,
): number {
  return sheerPowerLevel * 1;
}

export function getNewGamePlusStorageMultiplier(
  sheerPowerLevel: number,
): number {
  return Math.pow(1.5, sheerPowerLevel);
}

export function getNewGamePlusProductionMultiplier(
  sheerPowerLevel: number,
): number {
  return Math.pow(1.1, sheerPowerLevel);
}

export function getNewGamePlusChannelingAndSalePriceMultiplier(
  blazingSpeedLevel: number,
): number {
  return Math.pow(1.8, blazingSpeedLevel);
}

export function getNewGamePlusExpGainMultiplier(
  blazingSpeedLevel: number,
): number {
  return Math.pow(1.6, blazingSpeedLevel);
}

export function getNewGamePlusLootMultiplier(
  blazingSpeedLevel: number,
): number {
  return Math.pow(1.3, blazingSpeedLevel);
}

export function getNewGamePlusHpMultiplier(difficultyLevel: number): number {
  return difficultyLevel > 10 ? 200 : Math.pow(1.6, difficultyLevel);
}

export function getNewGamePlusAttackDelayMultiplier(
  difficultyLevel: number,
): number {
  return difficultyLevel > 10 ? 0.25 : Math.pow(0.9, difficultyLevel);
}

export function getNewGamePlusAccuracyAndDodgeMultiplier(
  difficultyLevel: number,
): number {
  return difficultyLevel > 10 ? 4.0 : Math.pow(1.1, difficultyLevel);
}

export function getNewGamePlusOtherStatMultiplier(
  difficultyLevel: number,
): number {
  return difficultyLevel > 10 ? 25 : Math.pow(1.3, difficultyLevel);
}

export function getNewGamePlusBoonPointsEarnedMultiplier(
  difficultyLevel: number,
): number {
  return Math.pow(1.6, difficultyLevel);
}

export function getStorylinesToUnlockForSpecificOracleLevel(
  storylineOracleLevel: number,
): Storyline[] {
  switch (storylineOracleLevel) {
    case 1:
      return [
        permacastStoryline,
        convenienceStoryline,
        Storylines.getById("terrainAdvantage"),
      ];
    case 2:
      return [
        TomeOfToxicKnowledge,
        Storylines.getById("tomeOfMentalKnowledge"),
        Storylines.getById("tomeOfLivingKnowledge"),
        Storylines.getById("tomeOfElectricalKnowledge"),
      ];
    case 3:
      return FURNITURE_PRESERVATION_OPTIONS.map((option) =>
        Storylines.getById("furniturePreservation-" + option.furniture.getId()),
      ).concat([Storylines.getById("theBigPurchase")]);
    case 4:
      return [
        Storylines.getById("schematics"),
        Storylines.getById("transmutationPrayer1"),
        TicTacToeStoryline,
      ];
    case 5:
      return [
        Storylines.getById("tomeOfDeathlyKnowledge"),
        Storylines.getById("tomeOfDivineKnowledge"),
        Storylines.getById("mysteriousBox"),
        Storylines.getById("starryNight"),
      ];
    case 6:
      return [
        Storylines.getById("varietyCollector"),
        Storylines.getById("boostingMachine"),
        Storylines.getById("transmutationPrayer2"),
        Storylines.getById("artisanOfMagic"),
      ];
    case 7:
      return valuesOfCached(SpellElement).map((element) =>
        Storylines.getById("masteryOf" + element),
      );
    case 8:
      return [
        CombatClassesStoryline,
        Storylines.getById("ritualistsSecret"),
        Storylines.getById("etherealTruthWeapon"),
        Storylines.getById("etherealTruthGarment"),
        Storylines.getById("etherealTruthFootwear"),
      ];
    case 9:
      return [
        Storylines.getById("blessedLand"),
        Storylines.getById("divineCombination"),
      ];
  }
  return [];
}

export function getStorylinesToAvoidForOracle10() {
  // Avoid any "ending" or "challenge"
  return Storylines.getAll().filter(
    (storyline) =>
      storyline.getId().startsWith("ending") ||
      storyline.getId().startsWith("challenge"),
  );
}

export function loadNewGamePlus() {
  // Backend transformations related to New Game Plus
  // Grant extra Boon Points if completed ending_4
  registerTransformation(
    [[TransformationTags.BoonPointsEarned]],
    "ending1BoonPointsEarned",
    t("World Not Cleared"),
    TransformationType.Multiplier,
    (state) => (!hasEverTriggeredEnding(state, "ending_1") ? 0 : 1.0),
  );

  registerTransformation(
    [[TransformationTags.BoonPointsEarned]],
    "ending4BoonPointsEarned",
    t("Transcended Humanity"),
    TransformationType.Multiplier,
    (state) => (hasEverTriggeredEnding(state, "ending_4") ? 2.0 : 1.0),
  );

  // Sheer Power
  registerTransformation(
    [[TransformationTags.MaxElementLevel]],
    "newGamePlusMaxLevel",
    t("Sheer Power level"),
    TransformationType.Addition,
    (state) => getNewGamePlusElementMaxLevelBonus(getSheerPowerLevel(state)),
  );
  registerTransformation(
    [[TransformationTags.MaxFamiliarLevel]],
    "newGamePlusMaxFamiliarLevel",
    t("Sheer Power level"),
    TransformationType.Addition,
    (state) => getNewGamePlusFamiliarMaxLevelBonus(getSheerPowerLevel(state)),
  );
  registerTransformation(
    [[TransformationTags.ItemQualityMax]],
    "newGamePlusMaxQuality",
    t("Sheer Power level"),
    TransformationType.Addition,
    (state) => getNewGamePlusItemQualityMaxBonus(getSheerPowerLevel(state)),
  );
  for (let resource in Resource) {
    if (resource != Resource.Mana && resource != Resource.TimePiece) {
      registerTransformation(
        [[resource, TransformationTags.Cap]],
        resource + "NewGamePlusStorage",
        t("Sheer Power level"),
        TransformationType.Multiplier,
        (state) => getNewGamePlusStorageMultiplier(getSheerPowerLevel(state)),
      );
    }
  }
  registerTransformation(
    [[TransformationTags.Production]],
    "newGamePlusProduction",
    t("Sheer Power level"),
    TransformationType.Multiplier,
    (state) => getNewGamePlusProductionMultiplier(getSheerPowerLevel(state)),
  );

  // Blazing Speed
  registerTransformation(
    [[TransformationTags.ExpGain], [TransformationTags.FamiliarExp]],
    "newGamePlusExpGain",
    t("Blazing Speed level"),
    TransformationType.Multiplier,
    (state) => getNewGamePlusExpGainMultiplier(getBlazingSpeedLevel(state)),
  );
  registerTransformation(
    [
      [TransformationTags.ChannelingEfficiency],
      [TransformationTags.ItemSalePrice],
    ],
    "newGamePlusChannelingAndSalePriceGain",
    t("Blazing Speed level"),
    TransformationType.Multiplier,
    (state) =>
      getNewGamePlusChannelingAndSalePriceMultiplier(
        getBlazingSpeedLevel(state),
      ),
  );
  registerTransformation(
    [[TransformationTags.Loot], [TransformationTags.LootChance]],
    "newGamePlusLootGain",
    t("Blazing Speed level"),
    TransformationType.Multiplier,
    (state) => getNewGamePlusLootMultiplier(getBlazingSpeedLevel(state)),
  );

  // World Difficulty
  registerTransformation(
    [[AttackTarget.Enemy, CombatStat.MaxHP]],
    "newGamePlusDifficultyHP",
    t("World Difficulty level"),
    TransformationType.Multiplier,
    (state) => getNewGamePlusHpMultiplier(getWorldDifficultyLevel(state)),
  );
  registerTransformation(
    [[AttackTarget.Enemy, CombatStat.AttackDelay]],
    "newGamePlusDifficultyAttackDelay",
    t("World Difficulty level"),
    TransformationType.Multiplier,
    (state) =>
      getNewGamePlusAttackDelayMultiplier(getWorldDifficultyLevel(state)),
  );
  registerTransformation(
    [
      [AttackTarget.Enemy, CombatStat.Attack],
      [AttackTarget.Enemy, CombatStat.Defense],
    ],
    "newGamePlusEnemyStats",
    t("World Difficulty level"),
    TransformationType.Multiplier,
    (state) =>
      getNewGamePlusOtherStatMultiplier(getWorldDifficultyLevel(state)),
  );
  registerTransformation(
    [
      [AttackTarget.Enemy, CombatStat.Accuracy],
      [AttackTarget.Enemy, CombatStat.Dodge],
    ],
    "newGamePlusEnemyStats",
    t("World Difficulty level"),
    TransformationType.Multiplier,
    (state) =>
      getNewGamePlusAccuracyAndDodgeMultiplier(getWorldDifficultyLevel(state)),
  );
  registerTransformation(
    [[TransformationTags.BoonPointsEarned]],
    "newGamePlusBoonPointsEarned",
    t("World Difficulty level"),
    TransformationType.Multiplier,
    (state) =>
      getNewGamePlusBoonPointsEarnedMultiplier(getWorldDifficultyLevel(state)),
  );
}
