import { t } from "../../../../utils/I18n";
import { getCombatVariable, setCombatVariable } from "../../../Flags";
import { GameState } from "../../../GameState";
import Wildgrass from "../../../items/material/Wildgrass";
import { SpellElement } from "../../../spells/Elements";
import { StrengthTemporaryEffect } from "../../../spells/fire/Strength";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  getCurrentEnemyHPFraction,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class BlazingTurtle extends Enemy {
  getId(): string {
    return "blazingTurtle";
  }

  getName(): string {
    return t("Blazing Turtle");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 700,
      attack: 40,
      defense: 32,
      attackDelay: 6.0,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./blazingTurtle.png");
  }

  getLevel(): number {
    return 4;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 55;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 5;
  }

  getNextAction(state: GameState): BattlerAction {
    if (
      getCurrentEnemyHPFraction(state) < 0.5 &&
      !getCombatVariable(state, "hasUsedFireball")
    ) {
      return {
        name: t("Fireball"),
        delay: this.getAttackDelay(state) * 3.0,
        transform: (state) => {
          state = standardEnemyAttackEffect(
            state,
            this.getAttack(state) * 2.0,
            {
              tags: [SpellElement.Fire],
            },
          );
          state = setCombatVariable("hasUsedFireball", true)(state);
          return state;
        },
      };
    }
    if (
      Math.random() < 0.2 &&
      !hasTemporaryEffect(
        state,
        StrengthTemporaryEffect.getId(),
        AttackTarget.Enemy,
      )
    ) {
      return {
        name: t("Strength"),
        delay: this.getAttackDelay(state) * 1.2,
        transform: (state) =>
          grantTemporaryEffect(StrengthTemporaryEffect.getId(), 30, {
            target: AttackTarget.Enemy,
            params: { commonBuff: { AttackMultiplier: 1.25 } },
          })(state),
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  isBoss(): boolean {
    return true;
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 2,
        chance: 1.0,
      },
    ];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "trainingTowerWater");
    return state;
  }

  getTips(): string {
    return t(
      "This guardian... It is literally a turtle in flames! Its attacks are extremely strong and easily pierce through any amount of defense, but they are very slow. It also has a fair amount of defense. Using effects that increase your maximum :hp: might be the best way to survive Fireball. But you will also need to have a good source of healing that you can repeat, because it does a lot of damage! A weapon with high :attack: might also be effective.",
    );
  }
}

export default new BlazingTurtle();
