import { t } from "../../../../utils/I18n";
import { GameState } from "../../../GameState";
import Wildgrass from "../../../items/material/Wildgrass";
import { StoneskinTemporaryEffect } from "../../../spells/earth/Stoneskin";
import {
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../../temporaryeffects/TemporaryEffects";
import { AttackTarget } from "../../AttackTarget";
import { setDungeonFloorCompleted } from "../../dungeons/Dungeons";
import {
  getCurrentEnemyHPFraction,
  standardEnemyAttackEffect,
} from "../../Exploration";
import { BattlerAction, BattlerStats, Enemy, EnemyLoot } from "../Enemy";

class RockGolem extends Enemy {
  getId(): string {
    return "rockGolem";
  }

  getName(): string {
    return t("Rock Golem");
  }

  getBaseStats(): BattlerStats {
    return {
      maxHP: 300,
      attack: 21,
      defense: 65,
      attackDelay: 3.5,
      accuracy: 100,
      dodge: 100,
      crit: 0,
    };
  }

  getPicture(state: GameState) {
    return require("./rockGolem.png");
  }

  getLevel(): number {
    return 4;
  }

  getCoinsAwardedBase(state: GameState): number {
    return 40;
  }

  getMonstiumAwardedBase(state: GameState): number {
    return 8;
  }

  getNextAction(state: GameState): BattlerAction {
    if (
      !hasTemporaryEffect(
        state,
        StoneskinTemporaryEffect.getId(),
        AttackTarget.Enemy,
      ) &&
      getCurrentEnemyHPFraction(state) < 0.5 &&
      Math.random() < 0.5
    ) {
      return {
        name: t("Stoneskin"),
        delay: this.getAttackDelay(state) * 1.0,
        transform: grantTemporaryEffect(StoneskinTemporaryEffect.getId(), 20, {
          target: AttackTarget.Enemy,
          params: { commonBuff: { DefenseMultiplier: 1.3 } },
        }),
      };
    }
    if (Math.random() < 0.3) {
      return {
        name: t("Stone Strike"),
        delay: this.getAttackDelay(state) * 1.3,
        transform: (state) =>
          standardEnemyAttackEffect(state, this.getAttack(state) * 1.5),
      };
    }
    return {
      name: t("Attack"),
      delay: this.getAttackDelay(state),
      transform: (state) =>
        standardEnemyAttackEffect(state, this.getAttack(state)),
    };
  }

  isBoss(): boolean {
    return true;
  }

  getItemsAwardedBase(state: GameState): EnemyLoot[] {
    return [
      {
        itemId: Wildgrass.getId(),
        amount: 2,
        chance: 1.0,
      },
    ];
  }

  onDeath(
    state: GameState,
    isFromCreatures: boolean,
    familiarId?: string,
  ): GameState {
    state = super.onDeath(state, isFromCreatures, familiarId);
    state = setDungeonFloorCompleted(state, "trainingTowerFire");
    return state;
  }

  getTips(): string {
    return t(
      "This moving Rock Golem is the guardian of the Tower of Fire Training. It has quite low maximum :hp:, but its :defense: is extremely high, and it can get it even higher! You will need weapons and spells that have a lot of :attack: to pierce through, such as axes or Fireball.",
    );
  }
}

export default new RockGolem();
