import { formatNumber } from "../../../../utils/FormattingUtils";
import { t } from "../../../../utils/I18n";
import { getBuildingAmount } from "../../../buildings/Buildings";
import { essenceCatalyst } from "../../../buildings/catalysts/EssenceCatalyst";
import { addGameStateListener } from "../../../GameStateListeners";
import { getResourceAmount, grantResource, Resource } from "../../../Resources";
import { Storyline } from "../../../storylines/Storyline";
import { Storylines } from "../../../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../../../transformation/Transformation";
import { TransformationTags } from "../../../transformation/TransformationTags";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { advertisingManual } from "../pretraining/AdvertisingManual";

export const outessencedStoryline = new Storyline(
  "outessenced",
  t("Outessenced"),
  290,
  25,
  (state) => t("Build at least 10 Essence Catalysts"),
  (state) =>
    t(
      "Obtain a bonus to channeling of half the effect of an Essence Catalyst for each Essence Catalyst you have less than the maximum you have ever had across any retirement ({{max}})",
      {
        max: formatNumber(
          state.world.maxFurnitureNumbers[essenceCatalyst.getId()],
        ),
      },
    ),
  (state) => advertisingManual.isCompleted(state),
);

export function loadOutessenced() {
  Storylines.register(outessencedStoryline);
  const eventBuilder = buildEvent("outessenced", t("(Storyline) Outessenced"), [
    EventTag.STORYLINE,
  ]).setStorylineIds(outessencedStoryline.getId());

  eventBuilder.message(
    t(`You finish putting together that last Essence Catalyst. They are so incredibly useful - each one of them has saved you so much work channeling Essence, you feel you cannot live without them!
    
"Excuse me," you hear from outside your door. "May I have a few moments of your attention?"

You take a peek, and you spot someone that seems... familiar: a man with a huge backpack. They're a traveling merchant! You get the feeling you might have seen him before.`),
  );
  eventBuilder.message(
    t(`"I happened to be walking through, and I heard something about some Essence Catalyst," he explains. "I'm well aware just how important these are to wizards of all skill levels."
      
"But wasn't it *difficult* to build them? Wasn't it slow, channeling all that :fireessence:? Well... *I have a solution!*"

The merchant pulls out a book from his bag. It reads: *"Outessenced"*. There is a drawing of an Essence Catalyst on the cover, but next to it, there appear to be many, many other Essence Catalysts drawn, except these are only outlined. The book catches your interest. What could this possibly be about?`),
  );
  eventBuilder
    .message(
      t(`"Ah, I see it's caught your eye," exclaims the merchant. "This was written by a celebrity professor of a school of magic. It describes a way to *record* Essence Catalysts somehow, so that they can be used even when they are no longer there... how is that even possible, I wouldn't know, I'm not a wizard," he says.
      
You are intrigued. That could be so useful - how much easier would it have been to set up your home if that was possible?! Until he says: "Very useful, isn't it? It can be yours for *500K:coins:*. And not a single one less!"

That's... a lot of :coins:, and you did already set up your Essence Catalysts, after all. You think again about whether it's worth buying the book...`),
    )
    .option(
      t("Buy the book (500K Coins)"),
      {
        transform: (state, params) =>
          grantResource(Resource.Coins, -500000)(state),
        isEnabled: (state, params) =>
          getResourceAmount(state, Resource.Coins) >= 500000,
      },
      "purchased",
    )
    .option("Maybe later")
    .end();
  eventBuilder
    .message(
      t(`You decide to purchase the book, after all that hard work building up your :coins:. You have a hunch this book could be valuable... as long as it does what the merchant said it did.
      
The merchant smiles while taking your :coins:. "Nice doing business with you," he says, then hands over the book. "Best of luck on your future furnishing!" He waves at you and walks away.

The book feels very light...`),
    )
    .tag("purchased")
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = outessencedStoryline.complete(state);
          state = outessencedStoryline.activateBonus(state);
          return state;
        },
      },
      "end",
    )
    .end();

  eventBuilder
    .message(
      t(`You skim through it. But you realize the book is actually light because it describes a simple concept. A *really* simple concept - that you weren't aware of at all!
      
You figure out you got scammed. There is no way this book was worth 500K:coins:. But on the other hand... you can tell right away that the idea works. And you can apply it right away!

**You have completed the "Outessenced" Storyline! From now on, you will gain a bonus to channeling of half the effect of an Essence Catalyst, for each Essence Catalyst you have less than the maximum you have ever had across all retirements! This will allow you to channel Essence a lot more effectively at the beginning of a run!**`),
    )
    .tag("end");

  const event = eventBuilder.build();

  addGameStateListener(event.getId(), (state) => {
    if (
      !event.hasTriggered(state) &&
      !outessencedStoryline.isCompleted(state) &&
      advertisingManual.isCompleted(state) &&
      getBuildingAmount(state, essenceCatalyst) >= 10
    ) {
      return triggerEvent(event);
    }
  });

  registerTransformation(
    [[TransformationTags.ChannelingEfficiency]],
    outessencedStoryline.getId(),
    outessencedStoryline.getName(),
    TransformationType.Multiplier,
    (state) => {
      if (!outessencedStoryline.isBonusActive(state)) {
        return 1.0;
      }
      const catalystAmount = getBuildingAmount(state, essenceCatalyst);
      const maxAmount =
        state.world.maxFurnitureNumbers[essenceCatalyst.getId()] ?? 0;
      if (catalystAmount < maxAmount) {
        const multiplierPerCatalyst =
          (essenceCatalyst.getPerCatalystBonusMultiplier(state) - 1) / 2 + 1;
        return Math.max(
          1.0,
          Math.pow(multiplierPerCatalyst, maxAmount - catalystAmount),
        );
      }
      return 1.0;
    },
  );
}
