import { t } from "../../../../utils/I18n";
import { Achievement } from "../../../Achievement";
import { registerAchievementUnlockerFromStoryline } from "../../../Achievements";
import { Flag } from "../../../Flag";
import { setFlag } from "../../../Flags";
import { Resource, getResourceAmount, grantResource } from "../../../Resources";
import { registerPostRetirementListener } from "../../../Retire";
import {
  addEnemyDeathListener,
  getSuccessfulExplorationsForFloor,
} from "../../../exploration/Exploration";
import SewerHideout from "../../../exploration/dungeons/sewers/SewerHideout";
import {
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../../../items/Inventory";
import { cotton } from "../../../items/transmute/materials/Cotton";
import { Storylines } from "../../../storylines/Storylines";
import { buildEvent } from "../../EventMessageBuilder";
import { triggerEvent } from "../../Events";
import { EventTag } from "../../GameEvent";
import { TicTacToeStoryline } from "./TicTacToeStoryline";

export function loadTicTacToe() {
  Storylines.register(TicTacToeStoryline);
  const ticTacToeEventBuilder = buildEvent(
    "ticTacToe",
    t("(Storyline) Tic-Tac-Toe"),
    [EventTag.STORYLINE],
  ).setStorylineIds(TicTacToeStoryline.getId());

  ticTacToeEventBuilder
    .message(
      t(`Before moving on, you notice an odd object on the floor. You take it in your hands. It is a grid-like wooden board, with 9 compartments on the top, set in a 3x3 grid. It is somewhat dirty, but the construction is sturdy and meticulous.
      
It's... a tic-tac-toe board, isn't it? For some reason, you get an urge to play. You decide to restore it to its proper usable state...`),
    )
    .option(
      t("Restore it (10 Cotton, 20K Water Essence)"),
      {
        transform: (state) => {
          state = grantResource(Resource.WaterEssence, -20000)(state);
          state = removeFromInventoryWithAnyParams(cotton, 10, state);
          return state;
        },
        isEnabled: (state) =>
          getResourceAmount(state, Resource.WaterEssence) >= 20000 &&
          getTotalAmountOfItem(cotton, state) >= 10,
      },
      "restored",
    )
    .option(t("Maybe later"))
    .end();

  ticTacToeEventBuilder
    .message(
      t(
        `You moisten the Cotton and rub it vigorously against the board. The dirty spots disappear! The board is ready to be used! You call the hired wizard. You get ready to start a game of tic-tac-toe with her, but you're struck with a big dilemma...`,
      ),
    )
    .tag("restored")
    .option(t('Choose to be "O"'), "O")
    .option(t('Choose to be "X"'), "X")
    .end();

  ticTacToeEventBuilder
    .message(
      t(`Of course! *O* is always for the winners. You get ready to write an O in the corner...

"What? *Tic-tac-toe*?" Your hired wizard looks at you, then at the board, and then furrows her brow. "...What are you doing with *that*? That's not a toy! That's a *Synchro Board*!"

You look at her, confused. *You've never heard of that in your life.*`),
    )
    .tag("O")
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = TicTacToeStoryline.complete(state);
          state = TicTacToeStoryline.activateBonus(state);
          state = setFlag(Flag.SynchroUnlocked)(state);
          return state;
        },
      },
      "end",
    )
    .end();
  ticTacToeEventBuilder
    .message(
      t(`Of course! *X* is always the superior side. You get ready to write an X in the corner...

"What? *Tic-tac-toe*?" Your hired wizard looks at you, then at the board, and then furrows her brow. "...What are you doing with *that*? That's not a toy! That's a *Synchro Board*!"

You look at her, confused. *You've never heard of that in your life.*`),
    )
    .tag("X")
    .option(
      t("Continue"),
      {
        transform: (state) => {
          state = TicTacToeStoryline.complete(state);
          state = TicTacToeStoryline.activateBonus(state);
          state = setFlag(Flag.SynchroUnlocked)(state);
          return state;
        },
      },
      "end",
    )
    .end();
  ticTacToeEventBuilder
    .message(
      t(`"You're meant to put *Elements* in the grid! Then, depending on what Elements and what position you put them on, you get different *bonuses*. These boards are *rare*!" She sounds pretty upset...
      
It takes you a few minutes to get over your desire to play, but once you do, you realize this could become another great tool for your journey... And maybe finding a good Element combination will scratch that same itch?

...Three Fire-in-a-row?

**You have completed the "Tic-Tac-Toe" Storyline! You have permanently unlocked the "Synchro" feature, where you can arrange Elements in a grid to get various bonuses. Find the best pattern and become more powerful!**`),
    )
    .tag("end")
    .end();

  const ticTacToeEvent = ticTacToeEventBuilder.build();

  addEnemyDeathListener(ticTacToeEvent.getId() + "Triggerer", (state) => {
    if (
      !ticTacToeEvent.hasTriggered(state) &&
      !TicTacToeStoryline.isCompleted(state) &&
      (getSuccessfulExplorationsForFloor(state, "sewerHideout") >= 6 ||
        SewerHideout.canFightBoss(state))
    ) {
      return triggerEvent(ticTacToeEvent)(state);
    }
    return state;
  });

  registerPostRetirementListener("synchroUnlocker", (state, isMock) => {
    if (TicTacToeStoryline.isBonusActive(state)) {
      return setFlag(Flag.SynchroUnlocked)(state);
    }
    return state;
  });

  registerAchievementUnlockerFromStoryline(
    TicTacToeStoryline,
    Achievement.TIC_TAC_TOE,
  );
}
