import { useCallback, useMemo, useState } from "react";
import { StyleSheet, View } from "react-native";
import { ScrollView } from "react-native-gesture-handler";
import { useTheme } from "react-native-paper";
import { getThemeByElementAndDarkMode } from "../../Themes";
import { ActionArea } from "../../backend/action/ActionAreas";
import { SpellElement } from "../../backend/spells/Elements";
import { Spell } from "../../backend/spells/Spell";
import { Spells, getVisibleSpellsByElement } from "../../backend/spells/Spells";
import { keysOf, useComponentDidUpdate } from "../../utils/CoreUtils";
import { formatNumber } from "../../utils/FormattingUtils";
import { SpellElementSelector } from "../spells/SpellElementSelector";
import { Button } from "./Button";
import Styles from "./Styles";
import { Text } from "./Text";
import { GameIcon } from "./icons/GameIcon";
import { useGameState } from "./useGameState";

function SpellSelectSpellButton(props: {
  spell: Spell;
  onPress: (spell: Spell) => void;
  theme: any;
}) {
  const state = useGameState();
  const spell = props.spell;
  const onSpellPress = useCallback(
    () => props.onPress(spell),
    [props.onPress, spell],
  );
  const spellDisplayName = spell.getBasicDisplayName(state);
  const spellCost = spell.getManaCost(state);
  const theme = props.theme;
  const buttonTitleStyles = useMemo(
    () => [
      {
        color: theme.colors.onPrimary,
      },
      {
        fontSize: 12,
      },
    ],
    [theme],
  );
  return useMemo(
    () => (
      <Button
        key={spell.getId()}
        compact={true}
        enableAutoRepress={true}
        onPress={onSpellPress}
        style={[Styles.flex]}
        labelStyle={{
          marginHorizontal: 16,
          flex: 1,
          marginVertical: 0,
          height: 40,
        }}
        contentStyle={{ marginHorizontal: 0, flex: 1, height: 40 }}
        // @ts-ignore I added this myself
        TextComponent={View}
        theme={theme}
      >
        <View style={styles.spellButtonTitleContainer}>
          <View style={styles.spellButtonTitleOnlyContainer}>
            <Text style={[buttonTitleStyles, Styles.flex]} numberOfLines={1}>
              {spellDisplayName}
            </Text>
          </View>
          <View
            style={[styles.spellButtonCostContainer, Styles.flexRowAlignCenter]}
          >
            {spellCost > 0 && (
              <View style={[Styles.flexRowAlignCenter, Styles.ml8]}>
                <Text style={buttonTitleStyles} doNotScaleFonts={true}>
                  {formatNumber(spellCost, { showDecimals: false })}
                </Text>
                <GameIcon icon="mana" style={styles.spellManaIcon} />
              </View>
            )}
          </View>
        </View>
      </Button>
    ),
    [spellDisplayName, buttonTitleStyles, spellCost, onSpellPress, theme],
  );
}

export function SpellSelect(props: {
  onSelect: (spellId: string) => void;
  spellId: string;
}) {
  const state = useGameState();

  const [spellId, setSpellId] = useState(props.spellId);
  const spell = Spells.getByIdNullable(spellId);

  const spellsByElement = getVisibleSpellsByElement(
    state,
    ActionArea.SPELL_MENU,
  );

  const elements =
    keysOf(getVisibleSpellsByElement(state, ActionArea.SPELL_MENU)) ?? [];

  const [selectedSubcategory, setSelectedSubcategory] = useState<SpellElement>(
    spell?.getElement?.(),
  );

  const spellsToShow =
    spellsByElement?.[selectedSubcategory as SpellElement] ?? [];

  const onSpellSelect = useCallback(
    (spell: Spell) => props.onSelect(spell.getId()),
    [props.onSelect],
  );

  useComponentDidUpdate(() => setSpellId(undefined), [selectedSubcategory]);

  const subcategoryPickerComponent = useMemo(
    () => (
      <SpellElementSelector
        selected={selectedSubcategory}
        onPress={setSelectedSubcategory}
        elements={elements}
        style={{ width: 216 }}
      />
    ),
    [selectedSubcategory, setSelectedSubcategory, [JSON.stringify(elements)]],
  );

  const theme = useTheme();
  const elementTheme =
    state.options.colorSpellButtonsByElement && selectedSubcategory != null
      ? getThemeByElementAndDarkMode(selectedSubcategory, theme.dark)
      : theme;

  const spellButtons = useMemo(
    () => (
      <View style={Styles.mv4}>
        {spellsToShow.map((spell) => (
          <View key={spell.getId()} style={Styles.mv4}>
            <SpellSelectSpellButton
              spell={spell}
              onPress={onSpellSelect}
              theme={elementTheme}
            />
          </View>
        ))}
      </View>
    ),
    [spellsToShow, onSpellSelect, elementTheme],
  );

  return (
    <View style={[Styles.flex, styles.topContainer]}>
      <View style={Styles.flexCenter}>
        {elements.length > 0 && subcategoryPickerComponent}
      </View>
      <View style={Styles.flex}>
        <ScrollView style={[{ flexGrow: 0 }, Styles.mt8]}>
          {spellButtons}
        </ScrollView>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  spellButtonTitleContainer: {
    flex: 1,
    flexDirection: "row",
    alignItems: "center",
  },
  spellButtonTitleOnlyContainer: {
    flex: 1,
    flexDirection: "row",
    justifyContent: "center",
    alignItems: "center",
  },
  spellButtonCostContainer: {
    flexDirection: "row",
    width: 100,
    justifyContent: "flex-end",
  },
  spellManaIcon: {
    height: 16,
    width: 16,
    justifyContent: "flex-end",
    alignItems: "center",
    marginLeft: 2,
  },
  topContainer: {
    minWidth: 300,
  },
});
