import { formatMultiplier } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ActionEffect } from "../action/Action";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatStat } from "../exploration/CombatStats";
import { ExplorationStatus } from "../exploration/ExplorationStatus";
import { getExplorationStatus } from "../exploration/ExplorationUtils";
import { FamiliarCompanionStatusType } from "../familiars/Familiar";
import { getCurrentCompanionData } from "../familiars/FamiliarUtils";
import { GameState } from "../GameState";
import { Storylines } from "../storylines/Storylines";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationValueType } from "../transformation/TransformationTags";
import { Ritual } from "./Ritual";
import { Rituals } from "./Rituals";

class NecromancyRitual extends Ritual {
  getId(): string {
    return "necromancyRitual";
  }
  getBaseName(): string {
    return t(`Necromancy Ritual`);
  }
  getDescription(state: GameState): string {
    return t("Your Familiars could gain unholy strength...");
  }
  protected getBaseRitualEffects(): Record<string, ActionEffect> {
    return {
      attackMagnitude: {
        value: 1.0,
        valueType: TransformationValueType.Percent,
      },
      regenMagnitude: {
        value: 5.0,
        valueType: TransformationValueType.Percent,
      },
    };
  }
  getEffect(state: GameState): string {
    const effects = this.getRitualEffects(state);
    const explanations = this.getRitualEffectExplanations(state);
    return t(
      `^{{attackMultiplier}}^<{{attackExplanation}}> Familiar :attack:; ^{{regenMultiplier}}^<{{regenExplanation}}> Familiar :hp: regeneration while recovering during combat`,
      {
        attackMultiplier: formatMultiplier(1.0 + effects.attackMagnitude),
        attackExplanation: explanations.attackMagnitude,
        regenMultiplier: formatMultiplier(1.0 + effects.regenMagnitude),
        regenExplanation: explanations.regenMagnitude,
      },
    );
  }
  getBaseCorruptionCost(): number {
    return 6;
  }
  getDeathLevelRequirement(): number {
    return 1;
  }
  isVisible(state: GameState): boolean {
    return Storylines.getById("forbiddenInvocation").isBonusActive(state);
  }
}

export const necromancyRitual = new NecromancyRitual();

export function loadNecromancyRitual() {
  Rituals.register(necromancyRitual);

  registerTransformation(
    [[AttackTarget.Familiar, CombatStat.Attack]],
    necromancyRitual.getId(),
    necromancyRitual.getBaseName(),
    TransformationType.Multiplier,
    (state) =>
      necromancyRitual.isActive(state)
        ? necromancyRitual.getRitualEffects(state, "attackMagnitude")
            .attackMagnitude + 1.0
        : 1.0,
  );

  registerTransformation(
    [[AttackTarget.Familiar, CombatStat.HPRegen]],
    necromancyRitual.getId(),
    necromancyRitual.getBaseName(),
    TransformationType.Multiplier,
    (state) =>
      necromancyRitual.isActive(state) &&
      getExplorationStatus(state) == ExplorationStatus.Combat &&
      getCurrentCompanionData(state)?.status?.companionStatus ==
        FamiliarCompanionStatusType.Reviving
        ? necromancyRitual.getRitualEffects(state, "regenMagnitude")
            .regenMagnitude + 1.0
        : 1.0,
  );
}

export function shouldShowGrayscaleFamiliar(state: GameState): boolean {
  return necromancyRitual.isActive(state);
}
