import memoize from "sonic-memoize";
import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import {
  ActionArea,
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { manaSteamers } from "../../enhancement/other/ManaSteamers";
import { SpellElement } from "../../spells/Elements";
import {
  IncomeOverTimeProducer,
  IncomeOverTimeProducers,
  clearCalculatedIncomeCache,
} from "../../timetick/IncomeOverTime";
import {
  applyTransformations,
  explainTransformationsText,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Building, BuildingSpell } from "../Building";
import {
  BuildingAmountListeners,
  getBuildingAmountTurnedOn,
} from "../Buildings";

const manaTags = [TransformationTags.Production, Resource.Mana, "manaSteamer"];
const BASE_REGEN = 6.0;
function getManaRegenPerSec(state: GameState): number {
  return applyTransformations(manaTags, state, BASE_REGEN);
}
const manaRegenUnit = memoize(() => ({
  unit: t(":mana:/sec"),
}));
function getManaRegenPerSecExplanation(state: GameState): string {
  return explainTransformationsText(
    manaTags,
    state,
    BASE_REGEN,
    manaRegenUnit(),
  );
}

const BASE_CONSUMPTION = 3.0;
const fireTags = [
  TransformationTags.Consumption,
  Resource.FireEssence,
  "manaSteamer",
];
function getFireEssenceConsumptionPerSec(state: GameState): number {
  return applyTransformations(fireTags, state, BASE_CONSUMPTION);
}
const fireEssenceUnit = memoize(() => ({
  unit: t(":fireessence:/sec"),
}));
function getFireEssenceConsumptionPerSecExplanation(state: GameState): string {
  return explainTransformationsText(
    fireTags,
    state,
    BASE_CONSUMPTION,
    fireEssenceUnit(),
  );
}

const waterTags = [
  TransformationTags.Consumption,
  Resource.WaterEssence,
  "manaSteamer",
];
function getWaterEssenceConsumptionPerSec(state: GameState): number {
  return applyTransformations(waterTags, state, BASE_CONSUMPTION);
}
const waterEssenceUnit = memoize(() => ({
  unit: t(":wateressence:/sec"),
}));
function getWaterEssenceConsumptionPerSecExplanation(state: GameState): string {
  return explainTransformationsText(
    waterTags,
    state,
    BASE_CONSUMPTION,
    waterEssenceUnit(),
  );
}

class ManaSteamer extends Building {
  getId(): string {
    return "manaSteamer";
  }

  getName(): string {
    return t("Mana Steamer");
  }

  getDisplayDescription(state: GameState): string {
    return t("A device that is able to use Water and Fire to produce Mana.");
  }

  getDisplayEffect(state: GameState): string {
    return t(
      "^+{{manaRegenPerSec}}^<{{manaRegenExplanation}}>:mana:/sec; ^-{{waterConsumptionPerSec}}^<{{waterConsumptionExplanation}}>:wateressence:/sec; ^-{{fireConsumptionPerSec}}^<{{fireConsumptionExplanation}}>:fireessence:/sec",
      {
        manaRegenPerSec: formatNumber(getManaRegenPerSec(state), {
          showDecimals: true,
        }),
        waterConsumptionPerSec: formatNumber(
          getWaterEssenceConsumptionPerSec(state),
          {
            showDecimals: true,
          },
        ),
        fireConsumptionPerSec: formatNumber(
          getFireEssenceConsumptionPerSec(state),
          {
            showDecimals: true,
          },
        ),
        manaRegenExplanation: getManaRegenPerSecExplanation(state),
        waterConsumptionExplanation:
          getWaterEssenceConsumptionPerSecExplanation(state),
        fireConsumptionExplanation:
          getFireEssenceConsumptionPerSecExplanation(state),
      },
    );
  }

  getBaseLandRequired(): number {
    return 4;
  }

  canTurnOff(): boolean {
    return true;
  }
}

const manaSteamer = new ManaSteamer();

class BuildManaSteamer extends BuildingSpell {
  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return { HOME: [ActionSubcategories.MANA] };
  }

  getBuilding(): Building {
    return manaSteamer;
  }

  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      WaterEssence: 3000,
      FireEssence: 3000,
    };
  }

  getBaseResourceScale(): Partial<Record<Resource, number>> {
    return {
      WaterEssence: 1.25,
      FireEssence: 1.25,
    };
  }

  isVisible(state: GameState): boolean {
    return manaSteamers.isPurchased(state) && super.isVisible(state);
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {};
  }

  getElement(): SpellElement {
    return SpellElement.Fire;
  }
}

const buildManaSteamer = new BuildManaSteamer();

IncomeOverTimeProducers.register(
  new IncomeOverTimeProducer(
    manaSteamer.getId(),
    manaSteamer.getName(),
    (state) => ({
      Mana:
        getManaRegenPerSec(state) *
        getBuildingAmountTurnedOn(state, manaSteamer),
      FireEssence:
        -1 *
        getFireEssenceConsumptionPerSec(state) *
        getBuildingAmountTurnedOn(state, manaSteamer),
      WaterEssence:
        -1 *
        getWaterEssenceConsumptionPerSec(state) *
        getBuildingAmountTurnedOn(state, manaSteamer),
    }),
  ),
);

BuildingAmountListeners.register((state, building) => {
  if (building == manaSteamer) {
    state = clearCalculatedIncomeCache(state);
  }
  return state;
});

export { buildManaSteamer, manaSteamer };
