export function isHealingPossible(
  state: GameState,
  target: AttackTarget,
): boolean {
  return (
    !hasTemporaryEffect(state, cursed.getId(), target) &&
    getCurrentHPFromTarget(state, target) > 0
  );
}

export function loadCommonTemporaryEffects() {
  registerTimeTickListener("CommonDoT", (state, timeElapsedSec) => {
    const temporaryEffects = getTemporaryEffects(
      state,
      (state, effect) => effect.params?.commonDoTAmount != null,
    );
    temporaryEffects.forEach((effect) => {
      const effectObject = TemporaryEffects.getById(effect.id);
      if (effect.target === AttackTarget.Enemy) {
        if (
          effect.params.commonDoTAmount < 0 &&
          getCurrentEnemyHP(state) <= 0
        ) {
          return state;
        }
        state = modifyEnemyCurrentHP(
          state,
          -effect.params.commonDoTAmount * timeElapsedSec,
        );
      } else if (effect.target === AttackTarget.Familiar) {
        if (
          effect.params.commonDoTAmount < 0 &&
          getCurrentFamiliarHP(state) <= 0
        ) {
          return state;
        }
        state = modifyCompanionCurrentHP(
          state,
          -effect.params.commonDoTAmount * timeElapsedSec,
          effectObject.getDisplayName(),
        );
      } else {
        if (
          effect.params.commonDoTAmount < 0 &&
          getCurrentPlayerHP(state) <= 0
        ) {
          return state;
        }
        state = modifyPlayerCurrentHP(
          state,
          -effect.params.commonDoTAmount * timeElapsedSec,
          effectObject.getDisplayName(),
        );
      }
    });
    return state;
  });

  valuesOf(AttackTarget).forEach((target) => {
    valuesOf(CombatStat).forEach((stat) => {
      registerTransformation(
        [[target, stat]],
        "CommonBuff" + target + stat,
        t("Buffs and debuffs"),
        TransformationType.Addition,
        (state) => {
          let totalValue = 0;
          const temporaryEffectList = getTemporaryEffectsForCommonBuff(
            stat,
            TransformationType.Addition,
          );
          temporaryEffectList.forEach((effect) => {
            const data = getTemporaryEffectData(state, effect.getId(), target);
            totalValue += data?.params?.commonBuff?.[stat] ?? 0;
          });
          return totalValue;
        },
      );
      registerTransformation(
        [[target, stat]],
        "CommonBuffMultiplication" + target + stat,
        t("Buffs and debuffs"),
        TransformationType.Multiplier,
        (state) => {
          let totalValue = 1.0;
          const multiplierStr = stat + "Multiplier";
          const temporaryEffectList = getTemporaryEffectsForCommonBuff(
            stat,
            TransformationType.Multiplier,
          );
          temporaryEffectList.forEach((effect) => {
            const data = getTemporaryEffectData(state, effect.getId(), target);
            totalValue *= data?.params?.commonBuff?.[multiplierStr] ?? 1.0;
          });
          return totalValue;
        },
      );
    });
  });

  registerTemporaryEffectListener(
    "TemporaryEffectCombatStatCacheClearer",
    (state, effect) => {
      // Blanket clear cache to save work
      const temporaryEffect = TemporaryEffects.getById(effect.id);
      const commonBuffs = temporaryEffect.isCommonBuffEnabled();
      if (commonBuffs.length > 0) {
        for (let commonBuff of commonBuffs) {
          state = clearCombatStatCache(state, effect.target, commonBuff.stat);
        }
      }
      return effect;
    },
  );

  // This deletes stray temporary effects at the end of each time tick, keeping things trimmed down
  registerTimeTickListener(
    "TemporaryEffectDeleterAndCacheCleaerer",
    (state, timeElapsedSec, isWarp) => {
      const secondsPlayed = getSecondsPlayed(state);
      const notClearedEffects = valuesOf(state.run.temporaryEffects).filter(
        (effect) => !effect.registeredClear && effect.endTime < secondsPlayed,
      );
      if (notClearedEffects.length > 0) {
        for (let effect of notClearedEffects) {
          effect.registeredClear = true;
          // Clear cache if an effect expired!
          state = clearCombatStatCache(state, effect.target);
          TemporaryEffectExpirationListeners.getAll().forEach((listener) =>
            listener(state, effect),
          );
        }
      }
      return state;
    },
    100000,
  );

  loadWet();
  loadChilled();
  loadFrozen();
  loadSeared();
  loadGuard();
  loadPoison();
  loadNoxiousCloak();
  loadShrunken();
  loadDisease();
  loadWeakened();
  loadGrounded();
  loadAirborne();
  loadCloakOfFlames();
  loadStunning();
  loadWizardDisarray();
  loadFrail();
  loadBerserk();
  loadPetrified();
  loadShapeshifted();
  loadPoisonImmunity();
  loadCorrosion();
  loadElementalProtection();
  loadCursed();
  loadStatusImmunity();
  loadParalyzed();
  loadResistAttackSpell();
  loadElementalSilence();
  loadSpecificStatusImmunity();
  loadDivineImmunity();
  loadResistAutoAttack();

  // Need to redo reflect
  // addGameStateListener(
  //   "EnemyReflect",
  //   (newState) => {
  //     if (!hasTemporaryEffect(newState, "EnemyReflect")) {
  //       return;
  //     }
  //     const isSpellAttack =
  //       newState.run.exploration.lastPlayerActionResult?.tags?.includes(
  //         TransformationTags.Spell,
  //       );
  //     const damage =
  //       newState.run.exploration.lastPlayerActionResult?.damage || 0;
  //     if (isSpellAttack && damage > 0) {
  //       return (state) => {
  //         state = modifyPlayerCurrentHP(state, -damage, "Spell Mirror");

  //         const actionResult = {
  //           id: Math.random().toString(),
  //           damage: damage,
  //           isHit: true,
  //           isCrit: false,
  //           time: getSecondsPlayed(state),
  //           target: AttackTarget.Player,
  //           tags: [],
  //         };
  //         state.run.exploration.lastEnemyActionResult = actionResult;
  //         state.run.exploration.actionResultQueue.push(actionResult);
  //         return state;
  //       };
  //     }
  //   },
  //   (state) => state.run.exploration.lastPlayerActionResult?.id ?? "none",
  // );
}
