import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { SpellElement } from "../Elements";
import { BasicChannelingSpellBase } from "./BasicChannelingSpellBase";

class ChannelAir extends BasicChannelingSpellBase {
  getId(): string {
    return "channelAir";
  }

  getSpellName(): string {
    return t("Channel Air");
  }

  getElement(): SpellElement {
    return SpellElement.Air;
  }

  getBaseEssenceEfficiency(): number {
    return 1.6;
  }

  getManaCostProportion(): number {
    return 0.05;
  }

  getDisplayDescription(state: GameState): string {
    return t("Channel some of your Mana into Air Essence, a basic resource.");
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Air: 1 };
  }
}

export default new ChannelAir();
