import { GameState } from "../GameState";
import { CombatActionResult } from "./ExplorationActionResult";
import { ExplorationStatus } from "./ExplorationStatus";

// This is meant to be used primarily inside enemy attack transforms
export function getLastQueuedActionResult(
  state: GameState,
): CombatActionResult | undefined {
  if (state.run.exploration.actionResultQueue.length == 0) {
    return undefined;
  }
  return state.run.exploration.actionResultQueue[
    state.run.exploration.actionResultQueue.length - 1
  ];
}

export function getExplorationStatus(state: GameState): ExplorationStatus {
  return state.run.exploration?.explorationStatus || ExplorationStatus.None;
}
