import React, { useContext, useMemo } from "react";
import { ThemeProvider, useTheme } from "react-native-paper";
import { VisibilityContext } from "./VisibilityContext";

export function GameStateFreezer(props: { isVisible: boolean; children: any }) {
  // const state = useGameState();
  const theme = useTheme();
  const visible = useContext(VisibilityContext);
  return useMemo(() => {
    return (
      <VisibilityContext.Provider value={props.isVisible && visible}>
        {/* <StateContext.Provider value={state}> */}
        {
          // @ts-ignore Seems to work
          <ThemeProvider theme={theme}>{props.children}</ThemeProvider>
        }
        {/* </StateContext.Provider> */}
      </VisibilityContext.Provider>
    );
  }, [
    // props.isVisible ? state.global.tickCount : false,
    props.isVisible && visible ? props.children : false,
    props.isVisible && visible ? theme : false,
  ]);
}
