import React, { useCallback, useMemo, useRef } from "react";
import { StyleSheet, View } from "react-native";
import { Flag } from "../backend/Flag";
import { hasFlag } from "../backend/Flags";
import {
  ActionArea,
  ActionSubcategories,
  translateActionSubcategory,
} from "../backend/action/ActionAreas";
import { getAllPurchasedEnhancements } from "../backend/enhancement/Enhancements";
import { keysOf } from "../utils/CoreUtils";
import { t } from "../utils/I18n";
import ActionsContainer from "./utility/ActionsContainer";
import CategoryContainer from "./utility/CategoryContainer";
import { CheckBox } from "./utility/CheckBox";
import { Markdown } from "./utility/Markdown";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { useGameState } from "./utility/useGameState";

function ObtainedEnhancementsView() {
  const state = useGameState();
  const alreadyObtainedEnhancements = getAllPurchasedEnhancements(state);
  return useMemo(
    () => (
      <View style={Styles.mt8}>
        {keysOf(alreadyObtainedEnhancements).length == 0 && (
          <Text key="none" style={{ fontStyle: "italic" }}>
            {t("You have not completed any enhancements yet!")}
          </Text>
        )}
        {keysOf(alreadyObtainedEnhancements).map((area, idx) => (
          <View key={area} style={idx > 0 && Styles.mt8}>
            <Text h3>
              {translateActionSubcategory(area as ActionSubcategories)}
            </Text>
            {alreadyObtainedEnhancements[area].map((enhancement) => (
              <View key={enhancement.getId()}>
                <Markdown>
                  {t("**{{enhancementName}}:** {{enhancementEffect}}", {
                    enhancementName: enhancement.getDisplayName(state),
                    enhancementEffect: enhancement.getDisplayEffect(state),
                  })}
                </Markdown>
              </View>
            ))}
          </View>
        ))}
      </View>
    ),
    [
      JSON.stringify(alreadyObtainedEnhancements),
      alreadyObtainedEnhancements.ENHANCE_STORAGE?.map((enhancement) => {
        return enhancement.getDisplayEffect(state);
      }).join(),
    ],
  );
}

function EnhancementConfigView() {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const autoPurchaseCallback = useCallback(() => {
    stateRef.current.apply((state) => {
      state.options.autoPurchaseEnhancements =
        !state.options.autoPurchaseEnhancements;
      return state;
    });
  }, []);

  return useMemo(
    () => (
      <View style={Styles.mt8}>
        <View>
          <CheckBox
            title={t("Auto purchase all")}
            checked={state.options.autoPurchaseEnhancements}
            onPress={autoPurchaseCallback}
            containerStyle={styles.checkboxContainer}
          />
        </View>
      </View>
    ),
    [state.options.autoPurchaseEnhancements, autoPurchaseCallback],
  );
}

export default function Enhance() {
  const state = useGameState();

  const purchasedComponent = useMemo(
    () => (
      <View style={Styles.mt16}>
        <CategoryContainer title={t("Already obtained")}>
          <ObtainedEnhancementsView />
        </CategoryContainer>
      </View>
    ),
    [],
  );

  const showAutoPurchase = hasFlag(state, Flag.AutoPurchaseUnlocked);

  return useMemo(
    () => (
      <View style={styles.campusContainer}>
        {showAutoPurchase && (
          <View style={Styles.mb16}>
            <CategoryContainer title={t("Enhancement Config")}>
              <EnhancementConfigView />
            </CategoryContainer>
          </View>
        )}
        <ActionsContainer area={ActionArea.ENHANCEMENT} />
        {purchasedComponent}
      </View>
    ),
    [purchasedComponent, showAutoPurchase],
  );
}

const styles = StyleSheet.create({
  campusContainer: {
    height: "100%",
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  checkboxContainer: {
    marginLeft: 0,
    marginTop: 8,
    padding: 0,
  },
});
