import { GameState, GameStateTransform } from "../GameState";
import { DataStore } from "../generic/DataStore";

export const GameStartupListeners = new DataStore<GameStateTransform>();

export function runGameStartupListeners(state: GameState): GameState {
  GameStartupListeners.getAll().forEach((listener) => {
    state = listener(state);
  });
  return state;
}
