import { formatMultiplier } from "../../../../../utils/FormattingUtils";
import { t } from "../../../../../utils/I18n";
import { ActionEffect } from "../../../../action/Action";
import { AttackTarget } from "../../../../exploration/AttackTarget";
import { GameState } from "../../../../GameState";
import { Resource } from "../../../../Resources";
import { SpellElement } from "../../../../spells/Elements";
import { poison } from "../../../../temporaryeffects/Poison";
import { registerTemporaryEffectListener } from "../../../../temporaryeffects/TemporaryEffects";
import { TransformationValueType } from "../../../../transformation/TransformationTags";
import { EquipmentSlot, getEquippedItem } from "../../../Equipment";
import { ItemParams } from "../../../Item";
import { EquipmentTransmutationSpell } from "../../../transmute/EquipmentTransmutationSpell";
import { EquippableItem } from "../../EquippableItem";

class CharmOfPoisonResistance extends EquippableItem {
  getId(): string {
    return "charmOfPoisonResistance";
  }
  getSlot(): EquipmentSlot {
    return EquipmentSlot.Accessory;
  }
  getPicture() {
    return require("./charmOfPoisonResistance.png");
  }
  getBaseName(params: ItemParams): string {
    return t("Charm of Poison Resistance");
  }
  getDescription(state: GameState, params: ItemParams): string | undefined {
    return t(
      "A necklace enchanted with protective magic. Effective against poison.",
    );
  }
  getDefenseBonusBase(params: ItemParams): number {
    return 1;
  }
  protected getBaseExtraItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 3.0,
        valueType: TransformationValueType.Divisor,
      },
    };
  }
  getEffectExtra(state: GameState, params: ItemParams): string {
    const effects = this.getItemEffects(state, params);
    const explanations = this.getItemEffectExplanations(state, params);
    return t("^{{magnitude}}^<{{explanation}}> damage received from Poison", {
      magnitude: formatMultiplier(1.0 / (1.0 + effects.magnitude)),
      explanation: explanations.magnitude,
    });
  }
  getBaseSalePrice(state: GameState, params: ItemParams): number {
    return 700;
  }
}

export const charmOfPoisonResistance = new CharmOfPoisonResistance();

registerTemporaryEffectListener(
  charmOfPoisonResistance.getId(),
  (state, temporaryEffectData) => {
    if (charmOfPoisonResistance.isEquippedWithAnyParams(state)) {
      if (
        temporaryEffectData.id == poison.getId() &&
        temporaryEffectData.target == AttackTarget.Player
      ) {
        const multiplier =
          1.0 /
          (1.0 +
            charmOfPoisonResistance.getItemEffects(
              state,
              getEquippedItem(state, EquipmentSlot.Accessory),
            ).magnitude);
        temporaryEffectData.params.magnitude =
          temporaryEffectData.params.magnitude * multiplier;
      }
    }
    return temporaryEffectData;
  },
);

class TransmuteCharmOfPoisonResistance extends EquipmentTransmutationSpell {
  getItem(): EquippableItem {
    return charmOfPoisonResistance;
  }
  getCraftingElementLevelRequirements(): Partial<Record<SpellElement, number>> {
    return {
      Poison: 4,
    };
  }
  getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return {
      resources: {
        Mana: 200,
        PoisonEssence: 2000,
      },
      items: {
        elementalShardPoison: 5,
      },
    };
  }
  getElement(): SpellElement {
    return SpellElement.Poison;
  }
}

export const transmuteCharmOfPoisonResistance =
  new TransmuteCharmOfPoisonResistance();
