import { keysOf } from "../../../utils/CoreUtils";
import { formatMultiplier, formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { Resource } from "../../Resources";
import { ActionEffect, DoActionArgs } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { TemporaryEffect } from "../../temporaryeffects/TemporaryEffect";
import {
  TemporaryEffectData,
  TemporaryEffects,
  getTemporaryEffectData,
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../temporaryeffects/TemporaryEffects";
import {
  TransformationType,
  registerTransformation,
} from "../../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../../transformation/TransformationTags";
import { SpellElement } from "../Elements";
import { Spell } from "../Spell";
import { RESOURCE_FOR_ELEMENT } from "../channeling/ChannelingResources";

class EnchantTransmutationTemporaryEffect extends TemporaryEffect {
  getId() {
    return "enchantTransmutation";
  }

  getDisplayName(): string {
    return t("Enchant Transmutation");
  }

  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    if (temporaryEffectData.params.magnitudeMana > 0) {
      return t(
        "-{{magnitudePct}}% Essences and :mana: required for transmutation",
        {
          magnitudePct: formatNumber(
            (1.0 - 1.0 / (temporaryEffectData.params.magnitude + 1.0)) * 100,
          ),
        },
      );
    }
    return t("-{{magnitudePct}}% Essences required for transmutation", {
      magnitudePct: formatNumber(
        (1.0 - 1.0 / (temporaryEffectData.params.magnitude + 1.0)) * 100,
      ),
    });
  }
}

const temporaryEffect = new EnchantTransmutationTemporaryEffect();
TemporaryEffects.register(temporaryEffect);

class EnchantTransmutation extends Spell {
  getId(): string {
    return "enchantTransmutation";
  }

  getSpellName(): string {
    return t("Enchant Transmutation");
  }

  getElement(): SpellElement {
    return SpellElement.Earth;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.GENERAL_BUFF;
  }

  getBaseManaCost(state: GameState): number {
    return 180;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 0.11112,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        valueType: TransformationValueType.Divisor,
      },
      magnitudeMana: {
        value: 0.11112,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        valueType: TransformationValueType.Divisor,
      },
      duration: {
        value: 15.0,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    const effects = this.getActionEffects(state);
    return grantTemporaryEffect(temporaryEffect.getId(), effects.duration, {
      params: {
        magnitude: effects.magnitude,
        magnitudeMana: effects.magnitudeMana,
      },
    })(state);
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Use the knowledge and power of the Earth element to make Transmutation more efficient.",
    );
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    if (effects.magnitudeMana > 0) {
      return t(
        "^{{magnitudePct}}^<{{magnitudeExplanation}}> Essences and :mana: required for Transmutation for ^{{duration}}^<{{durationExplanation}}> sec",
        {
          magnitudePct: formatMultiplier(1.0 / (1 + effects.magnitude)),
          magnitudeExplanation: explanations.magnitude,
          duration: formatNumber(effects.duration),
          durationExplanation: explanations.duration,
        },
      );
    }
    return t(
      "^{{magnitudePct}}^<{{magnitudeExplanation}}> Essences required for Transmutation for ^{{duration}}^<{{durationExplanation}}> sec",
      {
        magnitudePct: formatMultiplier(1.0 / (1 + effects.magnitude)),
        magnitudeExplanation: explanations.magnitude,
        duration: formatNumber(effects.duration),
        durationExplanation: explanations.duration,
      },
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Earth: 3 };
  }
}

const enchantTransmutation = new EnchantTransmutation();

registerTransformation(
  keysOf(SpellElement).map((element) => [
    TransformationTags.ItemCost,
    RESOURCE_FOR_ELEMENT[element],
  ]),
  temporaryEffect.getId(),
  enchantTransmutation.getSpellName(),
  TransformationType.Multiplier,
  (state) =>
    hasTemporaryEffect(state, enchantTransmutation.getId())
      ? 1.0 /
        (getTemporaryEffectData(state, temporaryEffect.getId()).params
          .magnitude +
          1.0)
      : 1.0,
);

registerTransformation(
  [[TransformationTags.ItemCost, Resource.Mana]],
  temporaryEffect.getId() + "mana",
  enchantTransmutation.getSpellName(),
  TransformationType.Multiplier,
  (state) =>
    hasTemporaryEffect(state, enchantTransmutation.getId())
      ? 1.0 /
        (getTemporaryEffectData(state, temporaryEffect.getId()).params
          .magnitudeMana +
          1.0)
      : 1.0,
);

export default enchantTransmutation;
