import { clone } from "../../utils/CoreUtils";
import { Achievement } from "../Achievement";
import { registerAchievementUnlocker } from "../Achievements";
import { getCurrentCombatClass } from "../classes/CombatClasses";
import { EndingStats, GameState, GameStateTransform } from "../GameState";
import { DataStore } from "../generic/DataStore";
import {
  getElementLevel,
  getMaxOfMaxPrimaryElementLevels,
  getPrimaryElements,
  maximumElementLevelExpMultiplier,
  maximumElementLevelProductionMultiplierDx,
} from "../spells/Elements";
import { Storylines } from "../storylines/Storylines";
import { addUICommand } from "../UICommands";

export type Ending = {
  id: string;
  title: string;
  description: string;
  storylineId: string;
};

export const Endings = new DataStore<Ending>();

export function addEnding(
  id: string,
  title: string,
  contents: string,
  storylineId: string,
): void {
  Endings.register({ id, description: contents, title, storylineId }, id);

  registerAchievementUnlocker(
    (state) => hasEverTriggeredEnding(state, id),
    id as Achievement,
  );
}

export function getEndingData(state: GameState): EndingStats | undefined {
  return state.run.endingTriggered;
}

export function hasEverTriggeredEnding(state: GameState, id: string): boolean {
  return state.world.endingsHistory.some((ending) => ending.endingId == id);
}

export const EndingCloseListeners = new DataStore<GameStateTransform>();

export function setGameEnding(state: GameState, id: string): GameState {
  if (
    state.run.endingTriggered != null &&
    state.run.endingTriggered.endingId === id
  ) {
    // Do nothing
    return state;
  }

  const ending = Endings.getById(id);
  const storyline = Storylines.getById(ending.storylineId);
  if (!storyline.isCompleted(state)) {
    state = storyline.complete(state);
  }

  const allStorylines = Storylines.getAll();
  const completedStorylines = allStorylines.filter((storyline) => {
    return storyline.isCompleted(state);
  });
  state.run.endingTriggered = {
    endingId: id,
    timestamp: Date.now(),
    challengeId: state.run.currentChallengeId,
    totalTimePlayed: state.global.secondsPlayed,
    totalTimePlayedThisWorld: state.world.secondsPlayed,
    totalTimePlayedThisRun: state.run.secondsPlayed,
    totalActiveTimePlayed: state.global.activeSecondsPlayed,
    totalActiveTimePlayedThisWorld: state.world.activeSecondsPlayed,
    totalActiveTimePlayedThisRun: state.run.activeSecondsPlayed,
    totalRealWorldTimePlayed: (Date.now() - state.global.startTimestamp) / 1000,
    totalRealWorldTimePlayedThisWorld:
      (Date.now() - state.world.startTimestamp) / 1000,
    totalRealWorldTimePlayedThisRun:
      (Date.now() - state.run.startTimestamp) / 1000,
    timePiecesConsumed: state.global.timePiecesConsumed,
    timePiecesConsumedThisWorld: state.world.timePiecesConsumed,
    timePiecesConsumedThisRun: state.run.timePiecesConsumed,
    totalRetirements: state.world.totalTimesResetted,
    primaryElement: getPrimaryElements(state)?.[0],
    primaryElementLevelOnEnding: getElementLevel(
      state,
      getPrimaryElements(state)?.[0],
    ),
    secondaryElement: getPrimaryElements(state)?.[1],
    secondaryElementLevelOnEnding:
      getPrimaryElements(state).length > 1
        ? getElementLevel(state, getPrimaryElements(state)?.[1])
        : undefined,
    combatClassId: getCurrentCombatClass(state)?.getId?.(),
    mpl: getMaxOfMaxPrimaryElementLevels(state),
    researchMultiplier: maximumElementLevelExpMultiplier(state),
    productionMultiplier: maximumElementLevelProductionMultiplierDx(state),
    completedStorylines: completedStorylines.length,
    worldsCreated: state.global.newGamePlusCount,
    sheerPowerLevel: state.world.sheerPowerLevel,
    storylineOracleLevel: state.world.storylineOracleLevel,
    blazingSpeedLevel: state.world.blazingSpeedLevel,
    worldDifficultyLevel: state.world.worldDifficultyLevel,
  };
  const endingsHistory = state.world.endingsHistory;
  endingsHistory.push(clone(state.run.endingTriggered));
  state.global.endingsHistory.push(clone(state.run.endingTriggered));
  state = addUICommand(state, "goToEnding");
  state = addUICommand(state, "closeAllMenus");
  state = addUICommand(state, "closeEventOverlay");
  return state;
}
