import { t } from "../../../utils/I18n";
import {
  ActionSubcategories,
  ActionSubcategory,
} from "../../action/ActionAreas";
import { GameState } from "../../GameState";
import { HealthPotion } from "../../items/equipment/pouch/HealthPotion";
import { Resource } from "../../Resources";
import {
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Enhancement } from "../Enhancement";
import { healthBoost } from "./HealthBoost";

class HealthPotionEffectivity extends Enhancement {
  getId(): string {
    return "healthPotionEffectivity";
  }
  getBaseName(): string {
    return t("Health Potion Effectivity");
  }
  getSubcategory(): ActionSubcategory {
    return ActionSubcategories.ENHANCE_EXPLORATION;
  }
  getBaseResourceCost(): Partial<Record<Resource, number>> {
    return {
      Monstium: 2000,
    };
  }
  getItemCost(): Record<string, number> {
    return {
      paper: 10,
    };
  }
  getRequiredEnhancements(): Enhancement[] {
    return [healthBoost];
  }
  getDisplayDescription(state: GameState): string {
    return t(
      "Enhance how you gain effects from Health Potions, to increase their effectivity.",
    );
  }
  getDisplayEffect(state: GameState): string {
    return t("Health Potions can be used 1 extra time per combat");
  }
}

export const healthPotionEffectivity = new HealthPotionEffectivity();

registerTransformation(
  [[HealthPotion.getId(), TransformationTags.PouchItemUses]],
  healthPotionEffectivity.getId(),
  healthPotionEffectivity.getBaseName(),
  TransformationType.Addition,
  (state) => (healthPotionEffectivity.isPurchased(state) ? 1 : 0),
);
