import { Entypo } from "@expo/vector-icons";
import React, { useCallback, useContext, useMemo, useState } from "react";
import { StyleSheet, TouchableWithoutFeedback, View } from "react-native";
import { useTheme } from "react-native-paper";
import { t } from "../../utils/I18n";
import Divider from "./Divider";
import { GameStateFreezer } from "./GameStateFreezer";
import { Text } from "./Text";
import { VisibilityContext } from "./VisibilityContext";

type CategoryContainerProps = {
  title: string;
  titleStyle?: any;
  containerStyle?: any;
  contentContainerStyle?: any;
  children: any;
  initiallyExpanded?: boolean;
  dividerColor?: string;
};

export function CategoryHeader(
  props: Omit<CategoryContainerProps, "children">,
) {
  return (
    <View style={[styles.category, props.containerStyle]}>
      <Text style={[styles.categoryTitle, props.titleStyle]} h4>
        {props.title}
      </Text>
      <Divider
        style={[
          styles.categoryDivider,
          props.dividerColor && { backgroundColor: props.dividerColor },
        ]}
      />
    </View>
  );
}

export default function CategoryContainer(props: CategoryContainerProps) {
  const [isExpanded, setExpanded] = useState(props.initiallyExpanded ?? true);
  const theme = useTheme();

  const visible = useContext(VisibilityContext);

  const setExpandedCallback = useCallback(() => {
    setExpanded(!isExpanded);
  }, [setExpanded, isExpanded]);

  const accessibilityLabel = isExpanded
    ? t("{{title}}, Expanded - Press to Collapse", { title: props.title })
    : t("{{title}}, Collapsed - Press to Expand", { title: props.title });

  const containerStyle = useMemo(
    () => [styles.category, props.containerStyle],
    [props.containerStyle],
  );
  const titleStyle = useMemo(
    () => [styles.categoryTitle, props.titleStyle],
    [props.titleStyle],
  );
  const contentContainerStyle = useMemo(
    () => [
      { display: isExpanded ? "flex" : "none" },
      props.contentContainerStyle,
    ],
    [isExpanded, props.contentContainerStyle],
  );
  const dividerStyle = useMemo(
    () => [
      styles.categoryDivider,
      props.dividerColor && { backgroundColor: props.dividerColor },
    ],
    [props.dividerColor],
  );

  const header = useMemo(
    () => (
      <View>
        <TouchableWithoutFeedback
          onPress={setExpandedCallback}
          accessibilityRole="header"
          accessibilityLabel={accessibilityLabel}
        >
          <View style={styles.categoryHeaderExpand}>
            <Text style={titleStyle} h4>
              {props.title}
            </Text>
            <Entypo
              name={isExpanded ? "chevron-down" : "chevron-right"}
              size={24}
              color={theme.colors.onBackground}
            />
          </View>
        </TouchableWithoutFeedback>
        <Divider style={dividerStyle} />
      </View>
    ),
    [
      accessibilityLabel,
      setExpandedCallback,
      titleStyle,
      props.title,
      isExpanded,
      theme,
      dividerStyle,
    ],
  );

  return (
    <View style={containerStyle}>
      {header}
      <GameStateFreezer isVisible={isExpanded && visible}>
        <View style={contentContainerStyle}>{props.children}</View>
      </GameStateFreezer>
    </View>
  );
}

const styles = StyleSheet.create({
  category: { paddingTop: 16 },
  categoryTitle: { fontWeight: "bold" },
  categoryDivider: { height: StyleSheet.hairlineWidth * 2 },
  categoryHeader: {
    height: 26,
  },
  categoryHeaderExpand: {
    height: 26,
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    cursor: "pointer",
  },
});
