import mem from "mem";
import memoize from "sonic-memoize";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { incrementAccumulatorVariable } from "../AccumulatorVariables";
import { GameState, GameStateTransform } from "../GameState";
import { addItemToQuickbar, isInQuickbar, isQuickbarFull } from "../Quickbar";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  grantResource,
} from "../Resources";
import { ActionEffect } from "../action/Action";
import {
  TransformationType,
  applyTransformations,
  applyTransformationsCached,
  applyTransformationsPermaCached,
  explainTransformationsText,
  registerTransformation,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import {
  addToInventory,
  getAmountOfItem,
  removeFromInventory,
} from "./Inventory";
import { ItemTagEnum } from "./ItemTagEnum";
import { isItemQualityUnlocked } from "./Items";

export type ItemTag = ItemTagEnum;

export function translateItemTag(tag: ItemTagEnum): string {
  switch (tag) {
    case ItemTagEnum.Consumable:
      return t("Consumable");
    case ItemTagEnum.Material:
      return t("Material");
    case ItemTagEnum.Familiar:
      return t("Familiar");
    case ItemTagEnum.Equipment:
      return t("Equipment");
    case ItemTagEnum.Hand:
      return t("Hand");
    case ItemTagEnum.Body:
      return t("Body");
    case ItemTagEnum.Accessory:
      return t("Accessory");
    case ItemTagEnum.Pouch:
      return t("Pouch");
    case ItemTagEnum.QuestItem:
      return t("Quest Item");
  }
}

export interface ItemStack {
  itemOccurrence: ItemOccurrence;
  amount: number;
}

export type ItemParams = Record<string, any>;

export interface ItemOccurrence {
  itemId: string;
  params: ItemParams;
}

export interface ItemAction {
  id: string;
  description: string;
  isEnabled: boolean;
  requireConfirm?: boolean;
  isActive?: boolean;
  transform?: GameStateTransform;
}

const saleActionOne = mem(
  (id: string, params: ItemParams, salePrice: number) => {
    return {
      id: "sell1" + salePrice,
      description: t(`Sell 1 ({{coins}}:coins:)`, {
        coins: formatNumber(salePrice),
      }),
      isEnabled: true,
      isActive: false,
      transform: (state: GameState) => {
        state = incrementAccumulatorVariable(state, "itemsSold", 1);
        state = removeFromInventory({ itemId: id, params }, 1, state);
        return grantResource(Resource.Coins, salePrice)(state);
      },
    };
  },
  { cacheKey: JSON.stringify },
);

const saleActionMany = mem(
  (
    id: string,
    params: ItemParams,
    salePrice: number,
    amount: number,
    isAll: boolean,
  ) => {
    let totalAmount = salePrice * amount;
    return {
      id: "sellMany" + (!isAll ? amount : "") + salePrice,
      description: isAll
        ? t(`Sell all ({{coins}}:coins:)`, {
            coins: formatNumber(totalAmount),
          })
        : t(`Sell {{amount}} ({{coins}}:coins:)`, {
            coins: formatNumber(totalAmount),
            amount: formatNumber(amount),
          }),
      isEnabled: true,
      requireConfirm: isAll,
      isActive: false,
      transform: (state: GameState) => {
        if (isAll) {
          amount = getAmountOfItem({ itemId: id, params }, state);
          totalAmount = salePrice * amount;
        }
        state = incrementAccumulatorVariable(state, "itemsSold", amount);
        state = removeFromInventory({ itemId: id, params }, amount, state);
        return grantResource(Resource.Coins, totalAmount)(state);
      },
    };
  },
  { cacheKey: JSON.stringify },
);

const quickbarAction = mem(
  (id: string, params: ItemParams, enabled: boolean) => {
    return {
      id: "quickbar" + enabled,
      description: t("Add to Quickbar"),
      isActive: false,
      isEnabled: enabled,
      transform: (state: GameState) => {
        return addItemToQuickbar(state, { itemId: id, params });
      },
    };
  },
  { cacheKey: JSON.stringify },
);

function getAmountNeededForCombining(state: GameState): number {
  return applyTransformationsPermaCached(
    [TransformationTags.ItemsNeededForCombining],
    state,
    3,
  );
}

const combineActionOne = mem(
  (id: string, params: ItemParams) => {
    return {
      id: "combineActionOne",
      description: t(`Combine 1 to +{{quality}}`, {
        quality: formatNumber((params?.itemQuality ?? 0) + 1),
      }),
      isEnabled: (state) =>
        getAmountOfItem({ itemId: id, params: params }, state) >=
        getAmountNeededForCombining(state),
      isActive: false,
      transform: (state: GameState) => {
        state = removeFromInventory(
          { itemId: id, params },
          getAmountNeededForCombining(state),
          state,
        );
        state = addToInventory(
          {
            itemId: id,
            params: { ...params, itemQuality: (params?.itemQuality ?? 0) + 1 },
          },
          1,
          state,
        );
        return state;
      },
    };
  },
  { cacheKey: JSON.stringify },
);

const combineActionMany = mem(
  (id: string, params: ItemParams, amount: number) => {
    return {
      id: "combineActionMany",
      description: t(`Combine all ({{amount}}) to +{{quality}}`, {
        quality: formatNumber((params?.itemQuality ?? 0) + 1),
        amount: formatNumber(amount),
      }),
      isEnabled: (state) => true,
      isActive: false,
      transform: (state: GameState) => {
        state = removeFromInventory(
          { itemId: id, params },
          amount * getAmountNeededForCombining(state),
          state,
        );
        state = addToInventory(
          {
            itemId: id,
            params: { ...params, itemQuality: (params?.itemQuality ?? 0) + 1 },
          },
          amount,
          state,
        );
        return state;
      },
    };
  },
  { cacheKey: JSON.stringify },
);

export const INITIAL_MAX_ITEM_QUALITY = 15;

const MAX_ITEM_QUALITY_TAGS = [TransformationTags.ItemQualityMax];

export function getMaxItemQuality(state: GameState): number {
  return applyTransformationsPermaCached(
    MAX_ITEM_QUALITY_TAGS,
    state,
    INITIAL_MAX_ITEM_QUALITY,
  );
}

export abstract class Item {
  constructor() {}

  abstract getId(): string;
  abstract getPicture(): any;
  abstract getBaseName(params: ItemParams): string;
  abstract getDescription(
    state: GameState,
    params: ItemParams,
  ): string | undefined;
  abstract getEffect(state: GameState, params: ItemParams): string | undefined;
  abstract getBaseSalePrice(state: GameState, params: ItemParams): number;

  getTags(): ItemTag[] {
    return [];
  }

  getName(params: ItemParams): string {
    const base = this.getBaseName(params);
    if (params?.itemQuality != null && params?.itemQuality > 0) {
      return `${base} +${formatNumber(params?.itemQuality)}`;
    }
    return `${base}`;
  }

  getDefaultName(): string {
    return this.getName(this.getDefaultParams());
  }

  protected getBaseItemEffects(
    params: ItemParams,
  ): Record<string, ActionEffect> {
    return {};
  }

  getCachedBaseItemEffects = memoize((params: ItemParams) =>
    this.getBaseItemEffects(params),
  );

  isActive(state: GameState): boolean {
    return false;
  }

  _tags: string[] | undefined;

  getTransformationTags() {
    if (this._tags == null) {
      this._tags = [this.getId(), ...this.getTags().map((itemTag) => itemTag)];
    }
    return this._tags;
  }

  tagsByEffect: Record<string, string[]> = {};
  getTagsByEffect(key: string, baseEffect: ActionEffect): string[] {
    if (this.tagsByEffect?.[key] == null) {
      this.tagsByEffect[key] = [
        ...this.getTags(),
        this.getId(),
        ...(baseEffect.tags ?? []),
        TransformationTags.ItemEffect,
      ];
    }
    return this.tagsByEffect[key];
  }

  _itemOccurrenceCache: Record<string, ItemOccurrence> = {};

  getMemoizedItemOccurrence = (params) => {
    const key = JSON.stringify(params);
    let cached = this._itemOccurrenceCache[key];
    if (cached == null) {
      cached = { itemId: this.getId(), params };
      this._itemOccurrenceCache[key] = cached;
    }
    // @ts-expect-error The transformations may add a "tags"
    if (cached.tags != null) {
      delete cached["tags"];
    }
    return cached;
  };

  getItemEffect(
    state: GameState,
    params: ItemParams,
    effectId: string,
    neutralValue?: number,
  ): number {
    const baseEffects = this.getCachedBaseItemEffects(params);
    const baseEffect = baseEffects[effectId];
    if (baseEffect == null || baseEffect?.value == neutralValue) {
      return neutralValue;
    }
    return applyTransformationsCached(
      this.getTagsByEffect(effectId, baseEffect),
      state,
      baseEffect.value,
      this.getMemoizedItemOccurrence(params),
    );
  }

  getItemEffects(
    state: GameState,
    params: ItemParams,
    onlyId?: string,
  ): Record<string, number> {
    const baseEffects = this.getCachedBaseItemEffects(params);
    const finalEffects: Record<string, number> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = applyTransformationsCached(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.getMemoizedItemOccurrence(params),
      );
    }
    return finalEffects;
  }

  explanationParams = memoize((params, baseEffect) => ({
    itemId: this.getId(),
    params: params,
    valueType: baseEffect.valueType,
    unit: baseEffect.unit,
  }));

  getItemEffectExplanations(
    state: GameState,
    params: ItemParams,
    onlyId?: string,
  ): Record<string, string> {
    const baseEffects = this.getCachedBaseItemEffects(params);
    const finalEffects: Record<string, string> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = explainTransformationsText(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.explanationParams(params, baseEffect),
      );
    }
    return finalEffects;
  }

  getSalePrice(
    state: GameState,
    params: ItemParams,
    extraTags?: string[],
  ): number {
    const basePrice = this.getBaseSalePrice(state, params);
    return applyTransformations(
      [
        TransformationTags.ItemSalePrice,
        ...this.getTransformationTags(),
        ...(extraTags ?? []),
      ],
      state,
      basePrice,
      this.getMemoizedItemOccurrence(params),
    );
  }

  getPrimaryAction(
    _state: GameState,
    _params: ItemParams,
  ): ItemAction | undefined {
    return undefined;
  }

  getSellOneAction(
    state: GameState,
    params: ItemParams,
    extraTags?: string[],
  ): ItemAction | undefined {
    const salePrice = this.getSalePrice(state, params, extraTags);
    const amount = getAmountOfItem(
      this.getMemoizedItemOccurrence(params),
      state,
    );

    return amount >= 1
      ? saleActionOne(this.getId(), params, salePrice)
      : undefined;
  }

  getSaleActions(state: GameState, params: ItemParams): ItemAction[] {
    const salePrice = this.getSalePrice(state, params);
    const amount = getAmountOfItem(
      this.getMemoizedItemOccurrence(params),
      state,
    );
    const remainingCoinCap =
      getResourceCap(state, Resource.Coins) -
      getResourceAmount(state, Resource.Coins);
    const canSellLess =
      remainingCoinCap < salePrice * amount &&
      salePrice > 0 &&
      remainingCoinCap > 0;
    const saleActions = [
      amount > 1 ? saleActionOne(this.getId(), params, salePrice) : null,
      canSellLess
        ? saleActionMany(
            this.getId(),
            params,
            salePrice,
            Math.ceil(remainingCoinCap / salePrice),
            false,
          )
        : null,
      saleActionMany(this.getId(), params, salePrice, amount, true),
    ];
    return saleActions.filter(
      (saleAction) => saleAction != null,
    ) as ItemAction[];
  }

  getQuickbarAction(
    state: GameState,
    params: ItemParams,
  ): ItemAction | undefined {
    if (!this.getPrimaryAction(state, params)) {
      return undefined;
    }
    return quickbarAction(
      this.getId(),
      params,
      !isInQuickbar(state, {
        type: "item",
        occurrence: this.getMemoizedItemOccurrence(params),
      }) && !isQuickbarFull(state),
    );
  }

  getCombineActions(state: GameState, params: ItemParams): ItemAction[] {
    const extraActions = [];
    if (
      this.isItemQualityValid() &&
      isItemQualityUnlocked(state) &&
      (params?.itemQuality ?? 0) < getMaxItemQuality(state)
    ) {
      const amountOfItem = getAmountOfItem(
        { itemId: this.getId(), params: params },
        state,
      );
      const neededItemsToCombine = getAmountNeededForCombining(state);
      if (amountOfItem >= neededItemsToCombine) {
        extraActions.push(combineActionOne(this.getId(), params));
      }
      if (amountOfItem >= neededItemsToCombine * 2) {
        extraActions.push(
          combineActionMany(
            this.getId(),
            params,
            Math.floor(amountOfItem / neededItemsToCombine),
          ),
        );
      }
    }
    return extraActions;
  }

  isItemQualityValid(): boolean {
    return false;
  }

  getExtraActions(state: GameState, params: ItemParams): ItemAction[] {
    return [];
  }

  getDefaultParams(): ItemParams {
    return {};
  }
}

registerTransformation(
  [[TransformationTags.ItemEffect]],
  "itemQualityBonus",
  t("Item Quality"),
  TransformationType.Multiplier,
  (state, params, previousValue) => {
    if (previousValue <= 0) {
      return 1.0;
    }
    return Math.pow(1.1, params?.params?.itemQuality ?? 0);
  },
);

registerTransformation(
  [[TransformationTags.ItemSalePrice]],
  "itemQualitySalePrice",
  t("Item Quality"),
  TransformationType.Multiplier,
  (state, params, previousValue) => {
    if (previousValue <= 0) {
      return 1.0;
    }
    return Math.pow(1.5, params?.params?.itemQuality ?? 0);
  },
);
