import color from "color";
import { useCallback, useMemo, useRef } from "react";
import { TouchableOpacity, View } from "react-native";
import { useTheme } from "react-native-paper";
import { setCurrentSynchroGrid } from "../../backend/synchro/Synchro";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { useGameState } from "../utility/useGameState";
import { SynchroGridSmallDisplay } from "./SynchroGridSmallDisplay";

export function SynchroGridSelector(props: { gridIndex: number }) {
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;

  const theme = useTheme();

  const onPress = useCallback(
    () =>
      stateRef.current.apply((state) => {
        state = setCurrentSynchroGrid(state, props.gridIndex);
        return state;
      }),
    [props.gridIndex],
  );

  const selected = props.gridIndex == state.world.synchro.currentGrid;

  return useMemo(
    () => (
      <TouchableOpacity
        onPress={onPress}
        accessible={true}
        accessibilityLabel={
          selected
            ? t("Selected - Select Grid {{index}}", {
                index: formatNumber(props.gridIndex + 1),
              })
            : t("Select Grid {{index}}", {
                index: formatNumber(props.gridIndex + 1),
              })
        }
        accessibilityRole="button"
      >
        <View
          style={{
            borderRadius: 4,
            backgroundColor: selected
              ? color(theme.colors.primary).alpha(0.25).toString()
              : undefined,
            padding: 4,
          }}
        >
          <SynchroGridSmallDisplay gridIndex={props.gridIndex} />
        </View>
      </TouchableOpacity>
    ),
    [onPress, selected, theme, props.gridIndex],
  );
}
