import { ExplorationTriggerGroups } from "../../events/autorandom/ExplorationTrigger";
import { DungeonFloors, Dungeons } from "./Dungeons";
import SewerEntrance from "./sewers/SewerEntrance";
import SewerHazardousSector from "./sewers/SewerHazardousSector";
import SewerHideout from "./sewers/SewerHideout";
import {
  loadSewerChests,
  sewers,
  sewersExplorationGroup,
} from "./sewers/Sewers";
import TrainingTowerAir from "./training/TrainingTowerAir";
import TrainingTowerEarth from "./training/TrainingTowerEarth";
import TrainingTowerFire from "./training/TrainingTowerFire";
import {
  loadTrainingTowerChests,
  trainingTowers,
  trainingTowersExplorationGroup,
} from "./training/TrainingTowers";
import TrainingTowerWater from "./training/TrainingTowerWater";

export function loadDungeonsDemo() {
  DungeonFloors.register(TrainingTowerFire);
  DungeonFloors.register(TrainingTowerEarth);
  DungeonFloors.register(TrainingTowerWater);
  DungeonFloors.register(TrainingTowerAir);
  Dungeons.register(trainingTowers);
  ExplorationTriggerGroups.register(trainingTowersExplorationGroup);
  loadTrainingTowerChests();

  DungeonFloors.register(SewerEntrance);
  DungeonFloors.register(SewerHazardousSector);
  DungeonFloors.register(SewerHideout);
  Dungeons.register(sewers);
  ExplorationTriggerGroups.register(sewersExplorationGroup);
  loadSewerChests();
}
