import memoize from "sonic-memoize";
import { GameState } from "../GameState";
import { ActionEffect } from "../action/Action";
import { DataStore } from "../generic/DataStore";
import { Identifiable } from "../generic/Identifiable";
import {
  applyTransformationsPermaCached,
  explainTransformationsText,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { CombatClassesStoryline } from "./CombatClassesStoryline";

export abstract class CombatClass implements Identifiable {
  abstract getId(): string;
  abstract getDisplayName(): string;
  abstract getDisplayDescription(state: GameState): string;
  abstract getDisplayEffect(state: GameState): string;

  isActive(state: GameState): boolean {
    return getCurrentCombatClass(state) == this;
  }

  isVisible(state: GameState): boolean {
    return isCombatClassSelectionUnlocked(state);
  }

  protected getBaseClassEffects(): Record<string, ActionEffect> {
    return {};
  }

  getCachedBaseClassEffects = memoize(() => this.getBaseClassEffects());

  tagsByEffect: Record<string, string[]> = {};
  getTagsByEffect(key: string, baseEffect: ActionEffect): string[] {
    if (this.tagsByEffect?.[key] == null) {
      this.tagsByEffect[key] = [
        ...(baseEffect.tags ?? []),
        TransformationTags.ClassEffect,
        key,
      ];
    }
    return this.tagsByEffect[key];
  }

  getClassEffects(state: GameState, onlyId?: string): Record<string, number> {
    const baseEffects = this.getCachedBaseClassEffects();
    const finalEffects: Record<string, number> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = applyTransformationsPermaCached(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
      );
    }
    return finalEffects;
  }

  getClassEffectExplanations(
    state: GameState,
    onlyId?: string,
  ): Record<string, string> {
    const baseEffects = this.getCachedBaseClassEffects();
    const finalEffects: Record<string, string> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = explainTransformationsText(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        {
          unit: baseEffect.unit,
          valueType: baseEffect.valueType,
        },
      );
    }
    return finalEffects;
  }
}

export const CombatClasses = new DataStore<CombatClass>();

export function isCombatClassSelectionUnlocked(state: GameState): boolean {
  return CombatClassesStoryline.isBonusActive(state);
}

export function getCurrentCombatClass(
  state: GameState,
): CombatClass | undefined {
  return state.run.combatClassId != null
    ? CombatClasses.getById(state.run.combatClassId)
    : undefined;
}

export function getSelectableCombatClasses(state: GameState): CombatClass[] {
  if (!isCombatClassSelectionUnlocked(state)) {
    return [];
  }
  return CombatClasses.getAll().filter((combatClass) =>
    combatClass.isVisible(state),
  );
}
