import memoize from "sonic-memoize";
import { keysOf } from "../../utils/CoreUtils";
import { GameState } from "../GameState";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  grantResource,
} from "../Resources";
import {
  getTotalAmountOfItem,
  removeFromInventoryWithAnyParams,
} from "../items/Inventory";
import { Items } from "../items/Items";
import { hasTemporaryEffect } from "../temporaryeffects/TemporaryEffects";
import {
  TransformationTag,
  applyTransformations,
  explainTransformationsText,
} from "../transformation/Transformation";
import {
  TransformationTags,
  TransformationValueType,
} from "../transformation/TransformationTags";
import { ActionArea, ActionSubcategory } from "./ActionAreas";

export type DoActionArgs = { isAutomatic: boolean };

export type ActionEffect = {
  value: number;
  tags?: TransformationTag[];
  unit?: string;
  valueType?: TransformationValueType;
  base?: number;
};

export abstract class Action {
  constructor() {}

  abstract getId(): string;
  abstract getDisplayName(state: GameState): string;
  abstract getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  };
  abstract doAction(args: DoActionArgs, state: GameState): GameState;
  abstract isVisible(state: GameState): boolean;
  abstract getDisplayDescription(state: GameState): string;
  abstract getDisplayEffect(state: GameState): string;

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    return {};
  }

  canSpam(): boolean {
    return true;
  }

  canAutoPurchaseMax(): boolean {
    return false;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {};
  }

  getCachedBaseActionEffects = memoize(() => this.getBaseActionEffects());

  tagsByEffect: Record<string, string[]> = {};
  getTagsByEffect(key: string, baseEffect: ActionEffect): string[] {
    if (this.tagsByEffect?.[key] == null) {
      this.tagsByEffect[key] = [
        ...this.getTags(),
        ...(baseEffect.tags ?? []),
        TransformationTags.ActionEffect,
        key,
      ];
    }
    return this.tagsByEffect[key];
  }

  getActionEffect(state: GameState, effectId: string): number {
    const baseEffects = this.getCachedBaseActionEffects();
    return applyTransformations(
      this.getTagsByEffect(effectId, baseEffects[effectId]),
      state,
      baseEffects[effectId].value,
      {
        action: this,
      },
    );
  }

  getActionEffects(state: GameState, onlyId?: string): Record<string, number> {
    const baseEffects = this.getCachedBaseActionEffects();
    const finalEffects: Record<string, number> = {};
    if (onlyId != null) {
      finalEffects[onlyId] = applyTransformations(
        this.getTagsByEffect(onlyId, baseEffects[onlyId]),
        state,
        baseEffects[onlyId].value,
        {
          action: this,
        },
      );
      return finalEffects;
    }
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = applyTransformations(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.memoizedActionEffectParam(),
      );
    }
    return finalEffects;
  }

  memoizedActionEffectParam = memoize(() => ({ action: this }));

  memoizedActionEffectExplanationParam = memoize(
    (baseEffect: ActionEffect) => ({
      action: this,
      unit: baseEffect.unit,
      valueType: baseEffect.valueType,
      base: baseEffect.base,
    }),
  );

  getActionEffectExplanations(
    state: GameState,
    onlyId?: string,
  ): Record<string, string> {
    const baseEffects = this.getCachedBaseActionEffects();
    const finalEffects: Record<string, string> = {};
    for (let key in baseEffects) {
      if (onlyId != null && key != onlyId) {
        continue;
      }
      const baseEffect = baseEffects[key];
      finalEffects[key] = explainTransformationsText(
        this.getTagsByEffect(key, baseEffect),
        state,
        baseEffect.value,
        this.memoizedActionEffectExplanationParam(baseEffect),
      );
    }
    return finalEffects;
  }

  getTags(): TransformationTag[] {
    const tags = [TransformationTags.Action, this.getId()];
    return tags;
  }

  canAfford(state: GameState): boolean {
    const cost = this.getCost(state);
    return (
      keysOf(cost.resources).every(
        (resource) =>
          getResourceAmount(state, resource) >=
            (cost.resources[resource] ?? 0) &&
          getResourceCap(state, resource) >= (cost.resources[resource] ?? 0),
      ) &&
      keysOf(cost.items).every(
        (itemId) =>
          getTotalAmountOfItem(Items.getById(itemId), state) >=
          cost.items[itemId],
      )
    );
  }

  isCapEnough(state: GameState): boolean {
    const cost = this.getCost(state);
    return (Object.keys(cost.resources) as Array<Resource>).every(
      (resource: Resource) => {
        return (
          (cost.resources[resource] ?? 0) <= getResourceCap(state, resource)
        );
      },
    );
  }

  payCost(state: GameState, n?: number): GameState {
    const cost = this.getCost(state);
    const times = n ?? 1;
    (Object.keys(cost.resources) as Array<Resource>).forEach((resourceName) => {
      state = grantResource(
        resourceName,
        -(cost?.resources?.[resourceName] ?? 0) * times,
      )(state);
    });
    (Object.keys(cost.items) as Array<string>).forEach((itemId) => {
      state = removeFromInventoryWithAnyParams(
        Items.getById(itemId),
        cost.items[itemId] * times,
        state,
      );
    });

    return state;
  }

  isEnabled(state: GameState, skipAffordabilityChecks?: boolean): boolean {
    if (skipAffordabilityChecks) {
      return true;
    }
    return this.canAfford(state);
  }

  // Returns true if the action is considered "active".
  // Used mainly for spells that give buffs, to display a visual aid.
  isActive(state: GameState): boolean {
    return hasTemporaryEffect(state, this.getId());
  }

  requireConfirmation(state: GameState): boolean {
    return false;
  }
}
