import React, {
  useCallback,
  useContext,
  useMemo,
  useRef,
  useState,
} from "react";
import { ScrollView, StyleSheet, View } from "react-native";
import { Menu, useTheme } from "react-native-paper";
import { ActionArea } from "../backend/action/ActionAreas";
import {
  getAutoPurchaseActions,
  isAutoPurchaseUnlocked,
} from "../backend/action/Actions";
import { getFirstHint } from "../backend/hints/Hints";
import { t } from "../utils/I18n";
import ActionsContainer from "./utility/ActionsContainer";
import { Button } from "./utility/Button";
import CategoryContainer from "./utility/CategoryContainer";
import { ProgressBar } from "./utility/ProgressBar";
import Styles from "./utility/Styles";
import { Text } from "./utility/Text";
import { VisibilityContext } from "./utility/VisibilityContext";
import { useGameState } from "./utility/useGameState";

export default function Home(props: {
  onScroll: (event: any) => void;
  scrollRef: any;
}) {
  const visible = useContext(VisibilityContext);
  const state = useGameState();
  const stateRef = useRef(state);
  stateRef.current = state;
  const currentHint = getFirstHint(state);
  const hintProgress = currentHint?.progress?.(state);

  const theme = useTheme();

  const [menuVisible, setMenuVisible] = useState(false);
  const toggleMenuVisible = useCallback(
    () => setMenuVisible(!menuVisible),
    [menuVisible, setMenuVisible],
  );

  const menuButton = useMemo(
    () => (
      <Button compact={true} onPress={toggleMenuVisible}>
        {t("More actions...")}
      </Button>
    ),
    [toggleMenuVisible],
  );
  const toggleAllAutoBuildAction = useMemo(
    () => (
      <Menu.Item
        key="toggleAllAutoBuild"
        title={t("Toggle all auto-purchase max")}
        onPress={() => {
          stateRef.current.apply((state) => {
            let shouldEnableAll = false;
            const actions = getAutoPurchaseActions();
            for (let action of actions) {
              if (
                action.isVisible(state) &&
                (action.getAreas()[ActionArea.HOME]?.length ?? 0) > 0 &&
                !(state.run.autoPurchaseActions[action.getId()] ?? false)
              ) {
                shouldEnableAll = true;
                break;
              }
            }
            for (let action of actions) {
              if (
                action.isVisible(state) &&
                (action.getAreas()[ActionArea.HOME]?.length ?? 0) > 0
              ) {
                state.run.autoPurchaseActions[action.getId()] = shouldEnableAll;
              }
            }
            return state;
          });
          setMenuVisible(false);
        }}
      />
    ),
    [setMenuVisible],
  );
  const menuDismiss = useCallback(
    () => setMenuVisible(false),
    [setMenuVisible],
  );
  const menuActions = useMemo(() => {
    const menuActions = [];
    if (isAutoPurchaseUnlocked(state)) {
      menuActions.push(toggleAllAutoBuildAction);
    }
    return menuActions;
  }, [toggleAllAutoBuildAction, isAutoPurchaseUnlocked(state)]);

  const menu = useMemo(() => {
    return (
      menuActions.length > 0 && (
        <Menu visible={menuVisible} anchor={menuButton} onDismiss={menuDismiss}>
          {menuActions}
        </Menu>
      )
    );
  }, [menuActions, menuVisible, menuButton, menuDismiss]);

  return useMemo(
    () => (
      <View style={styles.campusContainer}>
        <ScrollView
          contentContainerStyle={styles.contentContainer}
          style={styles.container}
          onScroll={visible && props.onScroll}
          ref={props.scrollRef}
        >
          <View style={[Styles.mt8, Styles.flexRowSpaceBetween]}>
            <Text h1>{t("Home")}</Text>
            {menu}
          </View>
          <ActionsContainer area={ActionArea.HOME} />
        </ScrollView>
        <View
          style={[
            styles.hintContainer,
            { borderColor: theme.colors.outlineVariant },
          ]}
        >
          <CategoryContainer
            titleStyle={styles.categoryTitle}
            title={t("Current Goal")}
            containerStyle={styles.categoryContainer}
          >
            <View style={styles.hintContents}>
              <Text style={styles.hintContentsText}>
                {currentHint?.description}!
              </Text>
              {currentHint?.progress && (
                <ProgressBar
                  progress={hintProgress}
                  style={styles.hintProgress}
                />
              )}
            </View>
          </CategoryContainer>
        </View>
      </View>
    ),
    [currentHint, hintProgress, theme, props.onScroll, visible, menu],
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    width: "100%",
  },
  contentContainer: {
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
    paddingHorizontal: 8,
  },
  hintContents: {
    marginLeft: 16,
    marginTop: 8,
    paddingRight: 32,
  },
  hintContentsText: {
    fontStyle: "italic",
  },
  hintProgress: {
    marginTop: 4,
  },
  hintContainer: {
    borderTopWidth: 1,
  },
  categoryTitle: {
    marginLeft: 8,
  },
  categoryContainer: {
    paddingTop: 4,
    marginBottom: 8,
  },
  campusContainer: {
    height: "100%",
    alignItems: "stretch",
    justifyContent: "flex-start",
    width: "100%",
  },
  campusHintWarning: {
    marginTop: 16,
  },
});
