import { useCallback, useRef, useState } from "react";
import { StateContextValue } from "../../backend/GameState";
import { Actions } from "../../backend/action/Actions";
import {
  SpellElement,
  getUnlockedElements,
} from "../../backend/spells/Elements";
import { GameStartupListeners } from "../../backend/spells/GameStartup";
import { keysOf } from "../../utils/CoreUtils";

export function useExpandedUIFromContext(
  state: StateContextValue,
  key: string,
  defaultValue: boolean,
): [boolean, (value: boolean) => void] {
  const stateRef = useRef(state);
  stateRef.current = state;

  const currentValue = state.options.saveExpandedUI
    ? state.options.expandedUI[key] ?? defaultValue
    : defaultValue;
  const [stateValue, setStateValue] = useState(currentValue);
  const setCurrentValue = useCallback(
    (value: boolean) => {
      setStateValue(value);
      stateRef.current.apply((s) => {
        s.options.expandedUI[key] = value;
        return s;
      });
    },
    [key, setStateValue],
  );

  return [stateValue, setCurrentValue];
}

GameStartupListeners.register((state) => {
  for (let key of keysOf(state.options.expandedUI)) {
    const action = Actions.getByIdNullable(key);
    if (action && !action.isVisible) {
      state.options.expandedUI[key] = false;
    }
    if (key.startsWith("study:")) {
      const element = key.split(":")[1];
      if (!getUnlockedElements(state).includes(element as SpellElement)) {
        state.options.expandedUI[key] = false;
      }
    }
  }
  return state;
}, "expandedUI");
