import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { formatNumber } from "../../utils/FormattingUtils";
import { Text } from "./Text";
import { useGameState } from "./useGameState";

export function DebugUPSOverlay(props: {}) {
  const state = useGameState();
  const showOverlay = state.global.debugShowUpdatesPerSec;
  if (!showOverlay) {
    return null;
  }
  const timestamps = state.global.debugLastUpdateTimestamps;
  if (timestamps.length < 2) {
    return null;
  }
  const startEndTimestamps = state.global.debugLastUpdateBeginEndTimestamps;
  if (startEndTimestamps.length < 1) {
    return null;
  }
  const msPerUpdate =
    (timestamps[timestamps.length - 1] - timestamps[0]) /
    (timestamps.length - 1);
  if (msPerUpdate <= 0) {
    return null;
  }
  const backendMsPerUpdate =
    startEndTimestamps
      .map((value) => value.end - value.begin)
      .reduce((prev, current) => prev + current, 0) / startEndTimestamps.length;
  const ticksPerUpdate =
    startEndTimestamps
      .map((value) => value.ticksRun)
      .reduce((prev, current) => prev + current, 0) / startEndTimestamps.length;
  const updatesPerSec = 1000 / msPerUpdate;
  const estimatedMsPerUpdate = msPerUpdate - 1000.0 / state.options.targetFps;
  return useMemo(
    () => (
      <View style={styles.overlay}>
        <View>
          <Text>
            Actual UPS: {formatNumber(updatesPerSec, { showDecimals: true })}
          </Text>
        </View>
        <View>
          <Text>
            Time/update:{" "}
            {formatNumber(estimatedMsPerUpdate, { showDecimals: true })}ms
          </Text>
        </View>
        <View>
          <Text>
            Backend ms:{" "}
            {formatNumber(backendMsPerUpdate, { showDecimals: true })}
            ms
          </Text>
        </View>
        <View>
          <Text>
            Ticks/update: {formatNumber(ticksPerUpdate, { showDecimals: true })}
          </Text>
        </View>
      </View>
    ),
    [Math.floor(Date.now() / 1000)],
  );
}

const styles = StyleSheet.create({
  overlay: {
    position: "absolute",
    right: 50,
    top: 50,
    opacity: 0.8,
    zIndex: 999999,
  },
});
