import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { clearLandCache } from "../../buildings/Buildings";
import { Flag } from "../../Flag";
import { hasFlag } from "../../Flags";
import { GameState } from "../../GameState";
import {
  clearResourceCapCache,
  grantResource,
  Resource,
} from "../../Resources";
import { Storylines } from "../../storylines/Storylines";
import {
  applyMultiplierTransformations,
  applyTransformations,
  registerTransformation,
  TransformationType,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { Action, DoActionArgs } from "../Action";
import { ActionArea, ActionSubcategories } from "../ActionAreas";

class ExpandHome extends Action {
  constructor() {
    super();
  }

  isVisible(state: GameState): boolean {
    return (
      hasFlag(state, Flag.LandPurchaseUnlocked) &&
      !Storylines.getByIdNullable("blessedLand")?.isBonusActive?.(state)
    );
  }

  getId(): string {
    return "expandHome";
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategories[]>> {
    return { HOME: [ActionSubcategories.ACTIONS] };
  }

  getDisplayName(state: GameState): string {
    const amount = state.run.landPurchaseCount;
    if (amount == 0) {
      return t("Expand Home");
    } else {
      return t("Expand Home ({{amount}})", { amount: formatNumber(amount) });
    }
  }

  getDisplayDescription(): string {
    return t(
      "Get a new parcel of land for your home so you can have more space for furniture.",
    );
  }

  landCapTags = [TransformationTags.Land, TransformationTags.Cap];
  getDisplayEffect(state: GameState): string {
    return t("+{{amount}}:land:", {
      amount: formatNumber(
        applyMultiplierTransformations(this.landCapTags, state, 20),
      ),
    });
  }

  getTags(): string[] {
    return [...super.getTags(), TransformationTags.Land];
  }

  costScaleTags = [...this.getTags(), TransformationTags.CostScale];

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    const fractionAddedPerLandPurchase = applyTransformations(
      this.costScaleTags,
      state,
      0.666667,
    );
    return {
      resources: {
        Coins:
          Math.pow(
            1.0 + fractionAddedPerLandPurchase,
            state.run.landPurchaseCount,
          ) * 1250,
      },
      items: {},
    };
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    if (!this.canAfford(state)) {
      return state;
    }
    state = grantResource(
      Resource.Coins,
      -(this.getCost(state).resources.Coins ?? 0),
    )(state);
    state.run.landPurchaseCount += 1;
    state.world.maxLandsPurchased = Math.max(
      state.run.landPurchaseCount,
      state.world.maxLandsPurchased,
    );
    state = clearLandCache(state);
    state = clearResourceCapCache(state);
    return state;
  }

  canAutoPurchaseMax(): boolean {
    return true;
  }
}

const expandHome = new ExpandHome();

registerTransformation(
  [[TransformationTags.Land, TransformationTags.Cap]],
  expandHome.getId(),
  t("Purchased Land"),
  TransformationType.Addition,
  (state, params) => 20 * state.run.landPurchaseCount,
);

export default expandHome;
