import memoize from "sonic-memoize";
import { fromEntries, keysOf } from "../../../utils/CoreUtils";
import { t } from "../../../utils/I18n";
import { cacheByTickCount } from "../../Caching";
import { Flag } from "../../Flag";
import { hasFlag, setFlag } from "../../Flags";
import { GameState, createHypotheticalState } from "../../GameState";
import { Resource, getResourceAmount } from "../../Resources";
import { DoActionArgs } from "../../action/Action";
import { ActionArea, ActionSubcategory } from "../../action/ActionAreas";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import {
  SpellElement,
  getElementExpByUsage,
  getTotalExpRequiredForLevel,
  getUnlockedElements,
  grantElementExp,
} from "../../spells/Elements";
import { Spell } from "../../spells/Spell";
import { SpellCastListeners } from "../../spells/SpellCastListeners";
import { Spells, registerSpell } from "../../spells/Spells";
import { applyTransformations } from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { addToInventory, getTotalAmountOfItem } from "../Inventory";
import { Item, ItemParams } from "../Item";
import { Items, isCraftingUnlocked } from "../Items";

export abstract class TransmutationSpell extends Spell {
  abstract getItem(): Item;

  getId(): string {
    return "transmute-" + this.getItem().getId();
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.TRANSMUTATION;
  }

  getAreas(): Partial<Record<ActionArea, ActionSubcategory[]>> {
    // Must populate by subclasses
    return { TRANSMUTE: this.getItem().getTags() };
  }

  getExtraTags(): string[] {
    return [TransformationTags.TransmutationSpell];
  }

  getSpellName(): string {
    return t("Transmute {{itemName}}", {
      itemName: this.getItem().getBaseName(this.getItem().getDefaultParams()),
    });
  }

  getDisplayName(state: GameState): string {
    return t("Transmute {{itemName}}", {
      itemName: this.getItem().getBaseName(this.getItem().getDefaultParams()),
    });
  }

  getCost(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    return this.getCraftingMaterials(state);
  }

  generateCraftedParams(state: GameState): ItemParams {
    const itemQuality = applyTransformations(this.tagsForQuality(), state, 0, {
      spell: this,
    });
    if (itemQuality == 0) {
      return {};
    }
    return { itemQuality };
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return this.getCraftingElementLevelRequirements();
  }

  abstract getCraftingElementLevelRequirements(): Partial<
    Record<SpellElement, number>
  >;

  // If crafting is NOT simplified, craft N times
  // If crafting is simplified, craft once and then multiply that result by N times
  // Because of item quality, simplified quality only really works for materials
  simplifyCrafting(): boolean {
    return true;
  }

  abstract getCraftingMaterialsBase(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  };

  tagsForResource = memoize((resource) => [
    ...this.getItem().getTags(),
    ...Object.keys(this.getCraftingElementLevelRequirements()),
    resource,
    this.getId(),
    TransformationTags.ItemCost,
  ]);

  tagsForItem = memoize((item) => [
    ...this.getItem().getTags(),
    ...Object.keys(this.getCraftingElementLevelRequirements()),
    item,
    this.getId(),
    TransformationTags.ItemCostItem,
  ]);

  tagsForQuality = memoize(() => [
    this.getId(),
    TransformationTags.ItemQuality,
    ...this.getItem().getTags(),
    ...Object.keys(this.getCraftingElementLevelRequirements()),
  ]);

  tagsForCreation = memoize(() => [
    this.getId(),
    TransformationTags.ItemAmountTransmuted,
    ...this.getItem().getTags(),
    ...Object.keys(this.getCraftingElementLevelRequirements()),
  ]);

  getCraftingMaterials(state: GameState): {
    resources: Partial<Record<Resource, number>>;
    items: Record<string, number>;
  } {
    const base = this.getCraftingMaterialsBase(state);
    let resources: Partial<Record<Resource, number>> = {},
      items: Record<string, number> = {};
    for (let resource in base.resources) {
      const baseAmount = base.resources[resource as Resource] ?? 0;
      resources[resource as Resource] = Math.ceil(
        applyTransformations(this.tagsForResource(resource), state, baseAmount),
      );
    }
    for (let item in base.items) {
      const baseAmount = base.items[item] ?? 0;
      items[item] = Math.ceil(
        applyTransformations(this.tagsForItem(item), state, baseAmount),
      );
    }
    return {
      resources,
      items,
    };
  }

  getDisplayDescription(state: GameState): string {
    return (
      this.getItem().getDescription(state, this.generateCraftedParams(state)) ??
      ""
    );
  }

  getDisplayEffect(state: GameState): string {
    return (
      this.getItem().getEffect(state, this.generateCraftedParams(state)) ?? ""
    );
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    throw new Error("Not used in transmutation spells");
  }

  canAfford(state: GameState): boolean {
    return super.canAfford(state);
  }

  hasCraftingMaterials(state: GameState): boolean {
    const materials = this.getCraftingMaterials(state);
    for (let resource in materials.resources) {
      const amount = materials.resources?.[resource as Resource] || 0;
      if (getResourceAmount(state, resource as Resource) < amount) {
        return false;
      }
    }

    for (let itemId in materials.items) {
      const amount = materials.items[itemId];
      if (getTotalAmountOfItem(Items.getById(itemId), state) < amount) {
        return false;
      }
    }

    return true;
  }

  canCraft(state: GameState): boolean {
    if (!this.isEnabled(state)) {
      return false;
    }

    if (!this.hasCraftingMaterials(state)) {
      return false;
    }

    return true;
  }

  getBaseManaCost(state: GameState): number {
    // Unused
    return 0;
  }

  isVisible(state: GameState): boolean {
    return isTransmutationUnlocked(state) && super.isVisible(state);
  }

  getExpFromUsage(state: GameState): Partial<Record<SpellElement, number>> {
    const elementRequirements = this.getCraftingElementLevelRequirements();
    const params = this.generateCraftedParams(state);

    return fromEntries(
      keysOf(elementRequirements).map((element) => {
        return [
          element,
          getElementExpByUsage(
            state,
            element,
            elementRequirements[element],
            this.getItem().getBaseSalePrice(state, params),
            true,
            this.getTags(),
          ),
        ];
      }),
    );
  }

  doAction(args: DoActionArgs, state: GameState): GameState {
    return this.craftN(state, args, 1);
  }

  calculateAmountPerCraft(state: GameState, args: DoActionArgs): number {
    return applyTransformations(this.tagsForCreation(), state, 1, {
      spell: this,
      automated: args.isAutomatic,
    });
  }

  craftN(state: GameState, args: DoActionArgs, n: number): GameState {
    if (!this.canAfford(state)) {
      return state;
    }

    if (!this.canCraft(state)) {
      return state;
    }

    let amountToCraft = this.calculateAmountPerCraft(state, args);

    amountToCraft *= n;

    if (this.simplifyCrafting()) {
      const params = this.generateCraftedParams(state);

      state = addToInventory(
        { itemId: this.getItem().getId(), params },
        amountToCraft,
        state,
      );
    } else {
      while (amountToCraft >= 1) {
        const params = this.generateCraftedParams(state);

        state = addToInventory(
          { itemId: this.getItem().getId(), params },
          1,
          state,
        );
        amountToCraft -= 1;
      }
    }

    state = this.payCost(state, n);

    const expGranted = this.getExpFromUsage(state);

    keysOf(expGranted).forEach((element) => {
      state = grantElementExp(element, expGranted[element] * n)(state);
    });

    for (let listener of SpellCastListeners.getAll()) {
      state = listener(state, this);
    }

    return state;
  }

  craftMax(state: GameState): GameState {
    const timesCraftable = this.timesCraftable(state);
    return this.craftN(state, { isAutomatic: false }, timesCraftable);
  }

  craftIntermediate(state: GameState): GameState {
    // First of all, check the materials
    const cost = this.getCost(state).items;
    for (let itemId in cost) {
      const item = Items.getById(itemId);
      const amountNeeded = cost[itemId];
      const amountInInventory = getTotalAmountOfItem(item, state);
      if (amountInInventory >= amountNeeded) {
        continue;
      }
      const spell = getTransmutationSpellForItem(item);
      if (spell == null) {
        continue;
      }
      if (!spell.isEnabled(state)) {
        continue;
      }
      // There is a spell that we can use, and we need the item
      const amountRequired = amountNeeded - amountInInventory;
      const amountPerCraft = spell.calculateAmountPerCraft(state, {
        isAutomatic: false,
      });
      const timesCraftable = spell.timesCraftable(state);
      const timesToCraft = Math.min(
        Math.ceil(amountRequired / amountPerCraft),
        timesCraftable,
      );
      state = spell.craftN(state, { isAutomatic: false }, timesToCraft);
    }
    return state;
  }

  // Are there any materials that are intermediate materials and transmutable,
  // and at least the materials for crafting 1 of them are held in hand?
  isCraftIntermediatePossible(state: GameState): boolean {
    const cost = this.getCost(state).items;
    for (let itemId in cost) {
      const item = Items.getById(itemId);
      const amountNeeded = cost[itemId];
      const amountInInventory = getTotalAmountOfItem(item, state);
      if (amountInInventory >= amountNeeded) {
        continue;
      }
      const spell = getTransmutationSpellForItem(item);
      if (spell == null || !spell.isVisible(state)) {
        continue;
      }
      if (!spell.isEnabled(state)) {
        continue;
      }
      return true;
    }
    return false;
  }

  // Are there any materials that are intermediate materials and transmutable,
  // even if the materials for them are not in hand?
  isCraftIntermediatePotentiallyPossible(state: GameState): boolean {
    const cost = this.getCost(state).items;
    for (let itemId in cost) {
      const item = Items.getById(itemId);
      const spell = getTransmutationSpellForItem(item);
      if (spell == null || !spell.isVisible(state)) {
        continue;
      }
      return true;
    }
    return false;
  }

  timesCraftable(state: GameState): number {
    const cost = this.getCost(state);
    const resourceThresholds = (
      Object.keys(cost.resources) as Array<Resource>
    ).map((resourceName) => {
      const amount = getResourceAmount(state, resourceName);
      return Math.floor(amount / cost.resources[resourceName]);
    });
    const itemThresholds = (Object.keys(cost.items) as Array<string>).map(
      (itemId) => {
        const amount = getTotalAmountOfItem(Items.getById(itemId), state);
        return Math.floor(amount / cost.items[itemId]);
      },
    );

    return Math.min(
      Math.min(...resourceThresholds),
      Math.min(...itemThresholds),
    );
  }

  protected shouldIgnoreCanCastSpell(): boolean {
    return true;
  }
}

export function isTransmutationUnlocked(state: GameState) {
  return hasFlag(state, Flag.TransmutationUnlocked);
}

const transmutationSpellsByItem: Record<string, TransmutationSpell> = {};

export function registerTransmutationSpellAndItem(spell: TransmutationSpell) {
  Items.register(spell.getItem());
  registerSpell(spell);
  transmutationSpellsByItem[spell.getItem().getId()] = spell;
}

export function getTransmutationSpellForItem(
  item: Item,
): TransmutationSpell | undefined {
  return transmutationSpellsByItem[item.getId()];
}

export function getVisibleTransmutationSpells(
  state: GameState,
): TransmutationSpell[] {
  if (!isCraftingUnlocked(state)) {
    return [];
  }

  return Spells.getAll().filter(
    (spell) => spell instanceof TransmutationSpell && spell.isVisible(state),
  ) as TransmutationSpell[];
}

export function getAllTransmutationSpellsEverCastable(
  state: GameState,
): TransmutationSpell[] {
  return cacheByTickCount(
    state,
    "allTransmutationSpellsEverCastable",
    (state) => {
      let hypotheticalState = createHypotheticalState(state);
      getUnlockedElements(hypotheticalState).forEach((element) => {
        hypotheticalState.run.elementExperience[element] =
          getTotalExpRequiredForLevel(
            hypotheticalState,
            state.world.maxElementLevels?.[element] ?? 1,
            element,
          );
      });
      hypotheticalState = setFlag(Flag.TransmutationUnlocked)(
        hypotheticalState,
      );
      return getVisibleTransmutationSpells(hypotheticalState);
    },
  );
}
