import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { CombatActionResultListeners } from "../exploration/ExplorationActionResult";
import { GameState } from "../GameState";
import { TransformationTags } from "../transformation/TransformationTags";
import { poison } from "./Poison";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  getTemporaryEffectData,
  grantTemporaryEffect,
  hasTemporaryEffect,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class NoxiousCloak extends TemporaryEffect {
  getId(): string {
    return "noxiousCloak";
  }
  getDisplayName(): string {
    return t("Noxious Cloak");
  }
  getIcon() {
    return require("./noxiousCloak.png");
  }
  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "Applies Poison (-{{magnitude}}:hp:/sec for {{duration}} sec) when hit",
      {
        magnitude: formatNumber(temporaryEffectData.params.magnitude, {
          showDecimals: true,
        }),
        duration: formatNumber(temporaryEffectData.params.duration),
      },
    );
  }
}

export const noxiousCloak = new NoxiousCloak();

export function loadNoxiousCloak() {
  TemporaryEffects.register(noxiousCloak);

  CombatActionResultListeners.register((state, result) => {
    if (
      (result.tags.includes(TransformationTags.AutoAttack) ||
        result.source == AttackTarget.Enemy ||
        result.source == AttackTarget.Familiar) &&
      hasTemporaryEffect(state, noxiousCloak.getId(), result.target)
    ) {
      const temporaryEffectData = getTemporaryEffectData(
        state,
        noxiousCloak.getId(),
        result.target,
      );
      state = grantTemporaryEffect(
        poison.getId(),
        temporaryEffectData.params.duration,
        {
          target: result.source,
          params: { magnitude: temporaryEffectData.params.magnitude },
        },
      )(state);
    }
    return state;
  });
}
