import { formatNumber } from "../../../utils/FormattingUtils";
import { t } from "../../../utils/I18n";
import { GameState } from "../../GameState";
import { addGameStateListener } from "../../GameStateListeners";
import { Resource, getResourceAmount, grantResource } from "../../Resources";
import { ActionEffect, DoActionArgs } from "../../action/Action";
import { SpellAutocastCategory } from "../../autocast/Autocast";
import { AttackTarget } from "../../exploration/AttackTarget";
import {
  CombatStat,
  clearCombatStatCache,
} from "../../exploration/CombatStats";
import { CombatActionResultListeners } from "../../exploration/ExplorationActionResult";
import { TemporaryEffect } from "../../temporaryeffects/TemporaryEffect";
import {
  TemporaryEffectData,
  TemporaryEffects,
  getTemporaryEffectData,
  grantTemporaryEffect,
  hasTemporaryEffect,
} from "../../temporaryeffects/TemporaryEffects";
import {
  TransformationType,
  registerTransformation,
} from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { CombatSpellBase } from "../CombatSpellBase";
import { SpellElement } from "../Elements";
import { registerEmpowerEffects } from "../Spells";

class EarthShieldTemporaryEffect extends TemporaryEffect {
  getId() {
    return "earthShield";
  }

  getDisplayName(): string {
    return t("Earth Shield");
  }

  getIcon() {
    return require("./stoneskin.png");
  }

  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t(
      "If :earthessence: is {{essenceAmount}} or more, +{{amount}}:defense:; -{{essenceAmount}}:earthessence: on hit",
      {
        amount: formatNumber(temporaryEffectData.params.effectAmount),
        essenceAmount: formatNumber(temporaryEffectData.params.essenceAmount),
      },
    );
  }
}

export const earthShieldTemporaryEffect = new EarthShieldTemporaryEffect();
TemporaryEffects.register(earthShieldTemporaryEffect);

const calculateEffect = (state) => {
  if (
    !hasTemporaryEffect(
      state,
      earthShieldTemporaryEffect.getId(),
      AttackTarget.Player,
    )
  ) {
    return 0;
  }
  const data = getTemporaryEffectData(
    state,
    earthShieldTemporaryEffect.getId(),
    AttackTarget.Player,
  );
  const essenceAmount = getResourceAmount(state, Resource.EarthEssence);
  if (essenceAmount < data.params.essenceAmount) {
    return 0;
  }
  return data.params.effectAmount;
};

registerTransformation(
  [[AttackTarget.Player, CombatStat.Defense]],
  earthShieldTemporaryEffect.getId(),
  earthShieldTemporaryEffect.getDisplayName(),
  TransformationType.Addition,
  calculateEffect,
);

addGameStateListener(
  earthShieldTemporaryEffect.getId() + "cacheClearer",
  (state) => (state) =>
    clearCombatStatCache(state, AttackTarget.Player, CombatStat.Defense),
  calculateEffect,
);

CombatActionResultListeners.register((state, result) => {
  if (
    !hasTemporaryEffect(
      state,
      earthShieldTemporaryEffect.getId(),
      AttackTarget.Player,
    ) ||
    result.target != AttackTarget.Player ||
    !result.isHit
  ) {
    return state;
  }
  const data = getTemporaryEffectData(
    state,
    earthShieldTemporaryEffect.getId(),
    AttackTarget.Player,
  );
  const essenceAmount = getResourceAmount(state, Resource.EarthEssence);
  if (essenceAmount < data.params.essenceAmount) {
    return state;
  }
  state = grantResource(
    Resource.EarthEssence,
    -data.params.essenceAmount,
  )(state);
  return state;
});

class EarthShield extends CombatSpellBase {
  getId(): string {
    return "earthShield";
  }

  getSpellName(): string {
    return t("Earth Shield");
  }

  getElement(): SpellElement {
    return SpellElement.Earth;
  }

  getAutocastCategory(): SpellAutocastCategory {
    return SpellAutocastCategory.COMBAT_BUFF;
  }

  getBaseManaCost(state: GameState): number {
    return 2000;
  }

  canUseOutsideOfCombat(): boolean {
    return true;
  }

  protected getBaseActionEffects(): Record<string, ActionEffect> {
    return {
      magnitude: {
        value: 30,
        tags: [TransformationTags.TemporaryEffectMagnitude],
        unit: ":defense:",
      },
      cost: {
        value: 1000,
        tags: [TransformationTags.TemporaryEffectExtraCost],
        unit: ":earthessence:",
      },
      duration: {
        value: 30.0,
        tags: [TransformationTags.TemporaryEffectDuration],
        unit: t(" sec"),
      },
    };
  }

  doSpellAction(state: GameState, args: DoActionArgs): GameState {
    const effects = this.getActionEffects(state);
    return grantTemporaryEffect(
      earthShieldTemporaryEffect.getId(),
      effects.duration,
      {
        params: {
          effectAmount: effects.magnitude,
          essenceAmount: effects.cost,
        },
      },
    )(state);
  }

  getDisplayDescription(state: GameState): string {
    return t(
      "Use stored :earthessence: to boost your resistance to all sorts of attacks.",
    );
  }

  getDisplayEffect(state: GameState): string {
    const effects = this.getActionEffects(state);
    const explanations = this.getActionEffectExplanations(state);
    return t(
      "If :earthessence: is ^{{essenceAmount}}^<{{essenceAmountExplanation}}> or more, ^+{{amount}}^<{{amountExplanation}}>:defense:; ^-{{essenceAmount}}^<{{essenceAmountExplanation}}>:earthessence: on hit; effects last ^{{duration}}^<{{durationExplanation}}> sec",
      {
        essenceAmount: formatNumber(effects.cost),
        essenceAmountExplanation: explanations.cost,
        amount: formatNumber(effects.magnitude),
        amountExplanation: explanations.magnitude,
        duration: formatNumber(effects.duration),
        durationExplanation: explanations.duration,
      },
    );
  }

  getLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Earth: 21 };
  }

  protected isEmpowerable(): boolean {
    return true;
  }

  getEmpoweringLevelRequirements(): Partial<Record<SpellElement, number>> {
    return { Earth: 58 };
  }
}

const earthShield = new EarthShield();

registerEmpowerEffects(
  earthShield,
  [TransformationTags.Cost, Resource.Mana],
  10,
);
registerEmpowerEffects(
  earthShield,
  [TransformationTags.TemporaryEffectExtraCost],
  1000,
);
registerEmpowerEffects(
  earthShield,
  [TransformationTags.TemporaryEffectMagnitude],
  4,
);

export { earthShield };
