import { t } from "../../utils/I18n";
import { GameState, GameStateTransform } from "../GameState";
import {
  addToInventory,
  getAmountOfItem,
  removeFromInventory,
} from "./Inventory";
import { ItemAction, ItemOccurrence } from "./Item";

import bind from "memoize-bind";
import { valuesOf } from "../../utils/CoreUtils";
import { clearResourceCapCache } from "../Resources";
import { clearCombatStatCache } from "../exploration/CombatStats";
import { DataStore } from "../generic/DataStore";
import { clearAllSpellCaches } from "../spells/Spells";
import { clearResearcherExpCache } from "../study/Study";
import { clearCalculatedIncomeCache } from "../timetick/IncomeOverTime";
import { applyTransformationsCached } from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { Items } from "./Items";
import { EquippableItem } from "./equipment/EquippableItem";
import { PouchItem } from "./equipment/PouchItem";

export enum EquipmentSlot {
  Hand = "Hand",
  Body = "Body",
  Accessory = "Accessory",
  Pouch = "Pouch",
}

export function translateEquipmentSlot(slot: EquipmentSlot): string {
  switch (slot) {
    case EquipmentSlot.Hand:
      return t("Hand");
    case EquipmentSlot.Body:
      return t("Body");
    case EquipmentSlot.Accessory:
      return t("Accessory");
    case EquipmentSlot.Pouch:
      return t("Pouch");
  }
}

const POUCH_SIZE_TAGS = [TransformationTags.PouchSize];
const ACCESSORY_SIZE_TAGS = [TransformationTags.AccessorySize];

export function getMaxEquippedItemsPerSlot(
  state: GameState,
  slot: EquipmentSlot,
): number {
  if (slot == EquipmentSlot.Pouch) {
    return applyTransformationsCached(POUCH_SIZE_TAGS, state, 4);
  }
  if (slot == EquipmentSlot.Accessory) {
    return applyTransformationsCached(ACCESSORY_SIZE_TAGS, state, 1);
  }
  return 1;
}

export function canUnequipItem(
  state: GameState,
  slot: EquipmentSlot,
  index: number,
): boolean {
  const items = getEquippedItems(state, slot);
  if (items.length <= index) {
    return false;
  }

  return true;
}

export function canEquipItem(
  state: GameState,
  slot: EquipmentSlot,
  item: ItemOccurrence,
): boolean {
  if (getAmountOfItem(item, state) <= 0) {
    return false;
  }

  if (getMaxEquippedItemsPerSlot(state, slot) == 0) {
    return false;
  }

  return true;
}

export const EquipmentChangeListeners = new DataStore<
  (state: GameState) => GameState
>();

export function equipToSlot(
  state: GameState,
  slot: EquipmentSlot,
  item: ItemOccurrence,
  position?: number,
  skipSave?: boolean,
): GameState {
  if (!canEquipItem(state, slot, item)) {
    return state;
  }

  const actualItem = Items.getById(item.itemId) as EquippableItem;

  const extraSlots = actualItem.getExtraSlots();

  const capacity = getMaxEquippedItemsPerSlot(state, slot);
  const equippedItems = getEquippedItems(state, slot);

  const equippedExtraSlotItem = getEquippedExtraSlotItem(state, slot, position);

  if (position == null) {
    // If we are under capacity, just push it to the end
    let done = false;
    for (
      let i = equippedExtraSlotItem != null && capacity > 1 ? 1 : 0;
      i < capacity;
      i++
    ) {
      if (equippedItems?.[i] == null) {
        position = i;
        done = true;
        break;
      }
    }
    if (!done) {
      position =
        equippedExtraSlotItem != null &&
        capacity > 1 &&
        slot != EquipmentSlot.Pouch
          ? 1
          : 0;
    }
  }

  if (slot != EquipmentSlot.Pouch) {
    // If there is already any slot that has this item, force position
    // to be that item
    // (cannot have 2 accessories of the same kind equipped)
    for (let i = 0; i < capacity; i++) {
      if (
        equippedItems?.[i] != null &&
        equippedItems[i].itemId == item.itemId
      ) {
        position = i;
        break;
      }
    }
  }

  // We unequip the item from the slot
  state = unequipFromSlotImpl(slot, position, skipSave, state);
  // Whatever we do, we must also remove the *first* item from any extra slots
  for (let extraSlot of extraSlots) {
    state = unequipFromSlotImpl(extraSlot, 0, skipSave, state);
  }

  // Then equip it
  equippedItems[position] = item;
  state.run.equipment[slot] = equippedItems;
  // And then finally remove from inventory
  state = removeFromInventory(item, 1, state);
  // And finally, if it's a pouch item, we mark it used
  if (slot == EquipmentSlot.Pouch) {
    state.run.exploration.pouchItemsUsesLeft[position] = 0;
  }

  if (!skipSave) {
    state = saveEquipmentLoadoutToPosition(
      state,
      state.run.lastEquipmentLoadoutModified,
    );
  }

  // We must clear all combat caches.
  // This is because equipment can have complex interactions. For example,
  // the Synergizer increases the effect of other equipment.
  state = clearCombatStatCache(state);
  state = clearResourceCapCache(state);
  state = clearCalculatedIncomeCache(state);
  state = clearResearcherExpCache(state);
  state = clearAllSpellCaches(state);

  EquipmentChangeListeners.getAll().forEach((listener) => {
    state = listener(state);
  });

  return state;
}

export function getEquippedItem(
  state: GameState,
  slot: EquipmentSlot,
  position?: number,
): ItemOccurrence | undefined {
  const item = state.run.equipment?.[slot];
  return item?.[position ?? 0];
}

export function getEquippedItems(
  state: GameState,
  slot: EquipmentSlot,
): ItemOccurrence[] {
  const items = state.run.equipment?.[slot] ?? [];
  return items;
}

export function getEquippedExtraSlotItem(
  state: GameState,
  slot: EquipmentSlot,
  position?: number,
): ItemOccurrence | undefined {
  const equipped = getEquippedItem(state, slot, position);
  if (equipped || (position ?? 0) > 0) {
    return equipped;
  }

  for (let otherSlot of valuesOf(EquipmentSlot)) {
    const capacity = getMaxEquippedItemsPerSlot(state, slot);
    for (let i = 0; i < capacity; i++) {
      const equippedItem = getEquippedItem(state, otherSlot, i);
      if (equippedItem) {
        const equippedActualItem = Items.getById(
          equippedItem.itemId,
        ) as EquippableItem;
        if (equippedActualItem.getExtraSlots().includes(slot)) {
          return equippedItem;
        }
      }
    }
  }
}

function unequipFromSlotImpl(
  slot: EquipmentSlot,
  position: number,
  skipSave: boolean,
  state: GameState,
): GameState {
  let item = getEquippedItem(state, slot, position);
  if (!item) {
    // Check if there might be another equipment item with this as its extra slot
    item = getEquippedExtraSlotItem(state, slot, position);
    if (!item) {
      return state;
    }
  }

  const actualItem = Items.getById(item.itemId) as EquippableItem;

  // If we are unequipping from pouch, clean up the effect
  if (slot == EquipmentSlot.Pouch) {
    state = (actualItem as PouchItem).doCleanup(state, item.params);
  }

  state = addToInventory(item, 1, state);
  state.run.equipment[actualItem.getSlot()][position] = null;

  if (!skipSave) {
    state = saveEquipmentLoadoutToPosition(
      state,
      state.run.lastEquipmentLoadoutModified,
    );
  }

  // We must clear all combat caches.
  // This is because equipment can have complex interactions. For example,
  // the Synergizer increases the effect of other equipment.
  state = clearCombatStatCache(state);
  state = clearResourceCapCache(state);
  state = clearCalculatedIncomeCache(state);
  state = clearResearcherExpCache(state);
  state = clearAllSpellCaches(state);

  EquipmentChangeListeners.getAll().forEach((listener) => {
    state = listener(state);
  });

  return state;
}

export function unequipFromSlot(
  this: any,
  slot: EquipmentSlot,
  position: number,
  skipSave?: boolean,
): GameStateTransform {
  // Do it this way to avoid creating extra functions and avoid rerenders
  return bind(unequipFromSlotImpl, this, slot, position, skipSave);
}

export function getUnequipAction(
  state: GameState,
  slot: EquipmentSlot,
  position: number,
): ItemAction {
  return {
    id: "unequip" + slot + position,
    description: t("Unequip"),
    isEnabled: canUnequipItem(state, slot, position),
    transform: unequipFromSlot(slot, position),
  };
}

export function saveEquipmentLoadoutToPosition(
  state: GameState,
  position: number,
  name?: string,
): GameState {
  const equipment = JSON.parse(JSON.stringify(state.run.equipment));
  state.run.equipmentLoadouts[position] = {
    name: name ?? t("Slot {{position}}", { position: position + 1 }),
    equipment: equipment,
  };
  state.run.lastEquipmentLoadoutModified = position;
  return state;
}

export function loadEquipmentLoadoutFromPosition(
  state: GameState,
  position: number,
): GameState {
  const equipment = JSON.parse(
    JSON.stringify(
      state.run.equipmentLoadouts?.[position]?.equipment || {
        Hand: [],
        Body: [],
        Accessory: [],
        Pouch: [],
      },
    ),
  );

  for (let slot of Object.values(EquipmentSlot)) {
    if (slot == EquipmentSlot.Pouch) {
      // Pouch items are NOT part of equipment loadouts, to avoid potions warding off
      continue;
    }
    const capacity = getMaxEquippedItemsPerSlot(state, slot);

    // First, unequip everything
    for (let i = 0; i < capacity; i++) {
      const currentItemOccurrence = getEquippedItem(
        state,
        slot as EquipmentSlot,
        i,
      );
      if (currentItemOccurrence != null) {
        state = unequipFromSlotImpl(slot as EquipmentSlot, i, true, state);
      }
    }

    // Then, equip everything
    for (let i = 0; i < capacity; i++) {
      const itemOccurrence = equipment?.[slot]?.[i];
      const currentItemOccurrence = getEquippedItem(
        state,
        slot as EquipmentSlot,
        i,
      );
      if (itemOccurrence == null && currentItemOccurrence != null) {
        // We want to unequip since there is nothing assigned to this slot
        if (!canUnequipItem(state, slot as EquipmentSlot, i)) {
          // Skip as there is no space to unequip
          continue;
        }
        state = unequipFromSlotImpl(slot as EquipmentSlot, i, true, state);
      } else {
        // We want to equip the new item
        if (itemOccurrence != null) {
          state = equipToSlot(
            state,
            slot as EquipmentSlot,
            itemOccurrence,
            i,
            true,
          );
        }
      }
    }
  }

  state.run.lastEquipmentLoadoutModified = position;
  return state;
}
