import React from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  getResourceInfo,
  translateResource,
} from "../../backend/Resources";
import { getTotalAmountOfItem } from "../../backend/items/Inventory";
import { Items } from "../../backend/items/Items";
import { RoundingMethod, formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Text } from "./Text";
import { GameIcon } from "./icons/GameIcon";
import { useGameState } from "./useGameState";

type Props = {
  resources: Partial<Record<Resource, number>>;
  items?: Record<string, number>;
};

function ResourceRow(props: {
  name: string;
  icon: string;
  current: number;
  cost: number;
  cap: number;
  switch?: boolean;
}) {
  const theme = useTheme();
  return (
    <View style={styles.row} accessible={true}>
      <View style={styles.inlineRow}>
        <GameIcon
          icon={props.icon}
          style={styles.icon}
          small
          accessibilityLabel=""
        />
        <Text>{t("{{itemOrResource}}: ", { itemOrResource: props.name })}</Text>
      </View>
      {!props.switch ? (
        <View style={styles.costValue}>
          <Text
            style={props.current < props.cost && { color: theme.colors.error }}
          >
            {formatNumber(props.current, {
              showDecimals: true,
              roundingMethod:
                props.current >= props.cost
                  ? RoundingMethod.UP
                  : RoundingMethod.DEFAULT,
            })}
          </Text>
          <Text>/</Text>
          <Text style={props.cap < props.cost && { color: theme.colors.error }}>
            {formatNumber(props.cost, {
              showDecimals: false,
              roundingMethod: RoundingMethod.UP,
            })}
          </Text>
        </View>
      ) : (
        <View style={styles.costValue}>
          <Text style={props.cap < props.cost && { color: theme.colors.error }}>
            {formatNumber(props.cost, {
              showDecimals: false,
              roundingMethod: RoundingMethod.UP,
            })}
          </Text>
          <Text>/</Text>
          <Text
            style={props.current < props.cost && { color: theme.colors.error }}
          >
            {formatNumber(props.current, {
              showDecimals: true,
              roundingMethod:
                props.current >= props.cost
                  ? RoundingMethod.UP
                  : RoundingMethod.DEFAULT,
            })}
          </Text>
        </View>
      )}
    </View>
  );
}

const ResourceRowMemo = React.memo(ResourceRow);

export { ResourceRowMemo as CostRow };

export function CostViewer(props: Props) {
  const state = useGameState();
  const resourceKeys = Object.keys(props.resources) as Array<Resource>;
  const rows = resourceKeys.map((resourceName) => {
    const resourceCost = props.resources[resourceName] as number;
    const resourceCap = getResourceCap(state, resourceName);
    const currentAmount = getResourceAmount(state, resourceName);
    const resourceInfo = getResourceInfo(resourceName);
    return (
      <ResourceRowMemo
        name={translateResource(resourceName)}
        icon={resourceInfo.icon}
        current={currentAmount}
        cost={resourceCost}
        cap={resourceCap}
        key={resourceName}
      />
    );
  });
  const itemRows = props.items
    ? Object.keys(props.items).map((itemId) => {
        const itemCost = props?.items?.[itemId] as number;
        const item = Items.getById(itemId);
        const itemAmount = getTotalAmountOfItem(item, state);
        return (
          <ResourceRowMemo
            name={item.getName(item.getDefaultParams())}
            icon={item.getId()}
            current={itemAmount}
            cost={itemCost}
            cap={1e10}
            key={itemId}
          />
        );
      })
    : [];
  return (
    <View style={styles.container}>
      {rows}
      {itemRows}
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    justifyContent: "center",
    alignItems: "stretch",
  },
  inlineRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginRight: 2,
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-end",
  },
  costValue: {
    minWidth: 80,
    flexDirection: "row",
    justifyContent: "flex-end",
  },
  notMet: {
    color: "red",
  },
});
