import { useEffect } from "react";
import { useGameState } from "../components/utility/useGameState";
import { electron, isElectron } from "./ElectronUtils";

export const ZOOM_SUPPORTED = isElectron();

export function ZoomChecker() {
  if (!isElectron()) {
    return null;
  }

  const state = useGameState();

  useEffect(() => {
    electron().webFrame.setZoomFactor(state.options.zoomLevel);
  }, [state.options.zoomLevel]);

  return null;
}
