import { valuesOf } from "../../utils/CoreUtils";
import { t } from "../../utils/I18n";
import { AttackTarget } from "../exploration/AttackTarget";
import { GameState } from "../GameState";
import { getSecondsPlayed } from "../timetick/TotalTimePlayed";
import {
  registerTransformation,
  TransformationType,
} from "../transformation/Transformation";
import { TransformationTags } from "../transformation/TransformationTags";
import { TemporaryEffect } from "./TemporaryEffect";
import {
  hasTemporaryEffect,
  registerTemporaryEffectListener,
  TemporaryEffectData,
  TemporaryEffects,
} from "./TemporaryEffects";

class DivineImmunityTemporaryEffect extends TemporaryEffect {
  getId() {
    return "divineImmunity";
  }

  getDisplayName(): string {
    return t("Divine Immunity");
  }

  isCombatOnly(): boolean {
    return true;
  }

  getIcon() {
    return require("./divineImmunity.png");
  }

  getDisplayDescription(
    state: GameState,
    temporaryEffectData: TemporaryEffectData,
  ): string {
    return t("Immune to all damage and debuffs");
  }
}

export const divineImmunityTemporaryEffect =
  new DivineImmunityTemporaryEffect();

export function loadDivineImmunity() {
  TemporaryEffects.register(divineImmunityTemporaryEffect);

  for (let target of valuesOf(AttackTarget)) {
    registerTransformation(
      [[TransformationTags.AttackDamageReceived, target]],
      "DivineImmunityDamage" + target,
      divineImmunityTemporaryEffect.getDisplayName(),
      TransformationType.Multiplier,
      (state) => {
        if (
          !hasTemporaryEffect(
            state,
            divineImmunityTemporaryEffect.getId(),
            target,
          )
        ) {
          return 1.0;
        }
        return 0;
      },
    );

    registerTemporaryEffectListener(
      "divineImmunityDebuffPrevention",
      (state, temporaryEffect) => {
        if (
          !hasTemporaryEffect(
            state,
            divineImmunityTemporaryEffect.getId(),
            temporaryEffect.target,
          )
        ) {
          return temporaryEffect;
        }
        const effect = TemporaryEffects.getById(temporaryEffect.id);
        if (effect.isBeneficial()) {
          return temporaryEffect;
        }
        // Prevent!
        temporaryEffect.endTime = getSecondsPlayed(state) - 1.0;
        return temporaryEffect;
      },
    );
  }
}
