import {
  Platform,
  StyleSheet,
  TouchableWithoutFeedback,
  View,
} from "react-native";
import {
  addActionToQuickbar,
  isInQuickbar,
  isQuickbarFull,
} from "../../backend/Quickbar";

import { Entypo, Ionicons } from "@expo/vector-icons";
import { useTheme } from "react-native-paper";
import { getThemeByElementAndDarkMode } from "../../Themes";
import { cacheByArbitraryVersion } from "../../backend/Caching";
import { Flag } from "../../backend/Flag";
import { hasFlag } from "../../backend/Flags";
import { StateContextValue } from "../../backend/GameState";
import { Resource, getResourceCap } from "../../backend/Resources";
import {
  addSpellToPermaCast,
  isAutocastUnlocked,
  isSpellInPermaCast,
  permaCastWizardPowerNeeded,
  permaCastWizardsNeeded,
  removeSpellFromPermaCast,
} from "../../backend/autocast/Autocast";
import {
  SpellElement,
  getPrimaryElements,
  getUnlockedElements,
  translateElement,
} from "../../backend/spells/Elements";
import { Spell } from "../../backend/spells/Spell";
import {
  addFavoriteSpell,
  isInFavoriteSpells,
  removeFromFavoriteSpells,
  toggleEmpowered,
} from "../../backend/spells/Spells";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { ActiveMark } from "./ActiveMark";
import { useAuxOptions } from "./AuxOptionsContext";
import { Button } from "./Button";
import { GameStateFreezer } from "./GameStateFreezer";
import { Markdown } from "./Markdown";
import { DescriptionViewer, elementColorByTheme } from "./Miscellaneous";
import { SpellWizardsInfo } from "./SpellWizardsInfo";
import Styles from "./Styles";
import { Text } from "./Text";
import { TooltipPositionType, TooltipWrapper } from "./Tooltip";
import { GameIcon } from "./icons/GameIcon";
import { useGameState } from "./useGameState";

export function SpellElementTag(props: { element: SpellElement }) {
  const spellElement = props.element;
  const state = useGameState();
  const theme = useTheme();
  return useMemo(
    () => (
      <View
        style={[
          styles.elementTextContainer,
          {
            backgroundColor: elementColorByTheme(theme.dark, spellElement),
          },
        ]}
      >
        <Text
          style={[
            getPrimaryElements(state).includes(spellElement) &&
              styles.primaryElementText,
            styles.spellElementBadgeText,
          ]}
        >
          {spellElement}
        </Text>
      </View>
    ),
    [spellElement, getPrimaryElements(state).join(), theme],
  );
}

type SpellViewProps = {
  spell: Spell;
  tooltipExtra?: string;
  showAddToQuickbar?: boolean;
  showWizards?: boolean;
  collapsed?: boolean;
  toggleCollapsed?: () => void;
  isMinimal?: boolean;
  onPress?: (spell: Spell) => void;
};

function SpellNameText(props: { spell: Spell }) {
  const state = useGameState();
  const spell = props.spell;
  return <>{spell.getDisplayName(state)}</>;
}

function SpellViewButtonContents(props: { spell: Spell; theme: any }) {
  const spell = props.spell;
  const theme = props.theme;
  const state = useGameState();
  const disabled = !spell.isEnabled(state);
  const buttonTitleStyles = useMemo(
    () => [
      {
        color: theme.colors.onPrimary,
      },
      disabled && {
        color: theme.colors.onSurfaceDisabled,
      },
      styles.smallButtonTitle,
    ],
    [theme, disabled],
  );
  const spellDisplayName = useMemo(
    () => <SpellNameText spell={spell} />,
    [spell],
  );
  const spellCost = spell.getCost(state)?.resources?.Mana ?? 0;
  const isActive = spell.isActive(state);
  const buttonContents = useMemo(
    () => (
      <View style={styles.spellButtonTitleContainer}>
        <View style={styles.spellButtonTitleOnlyContainer}>
          {isActive && <ActiveMark disabled={disabled} themeOverride={theme} />}
          <View style={styles.spellButtonTitleTitleOnlyContainer}>
            <Text style={buttonTitleStyles} numberOfLines={2}>
              {spellDisplayName}
            </Text>
          </View>
        </View>
        <View
          style={[styles.spellButtonCostContainer, Styles.flexRowAlignCenter]}
        >
          <Text style={buttonTitleStyles} doNotScaleFonts={true}>
            {formatNumber(spellCost, { showDecimals: false })}
          </Text>
          <GameIcon icon="mana" style={styles.spellManaIcon} />
        </View>
      </View>
    ),
    [isActive, disabled, buttonTitleStyles, spellDisplayName, spellCost],
  );
  return buttonContents;
}

function SpellViewMemoized(
  props: SpellViewProps & { state: StateContextValue },
) {
  const state = props.state;
  const stateRef = useRef(props.state);
  const spell = props.spell;
  const showAddToQuickbar = props.showAddToQuickbar || false;

  let displayDescription = "",
    displayEffect = "";

  if (!props.collapsed || Platform.OS === "web") {
    [displayDescription, displayEffect] = cacheByArbitraryVersion(
      state,
      "spellDescriptionAndEffect:" + spell.getId(),
      (state) => [
        spell.getDisplayDescription(state),
        spell.getDisplayEffect(state),
      ],
      (state) =>
        Math.floor(Date.now() / 500).toString() + spell.isEmpowered(state),
    );
  }

  const descriptionContainer = useMemo(() => {
    const descriptionContents = `${displayDescription} **${displayEffect}**`;
    return (
      <View style={styles.descriptionTextContainer}>
        <DescriptionViewer style={{ flex: 1 }}>
          {descriptionContents}
        </DescriptionViewer>
      </View>
    );
  }, [displayDescription, displayEffect]);

  const theme = useTheme();

  let spellElement: SpellElement | undefined = spell.getElement();
  if (spellElement && !getUnlockedElements(state).includes(spellElement)) {
    spellElement = undefined;
  }
  const elementText = useMemo(() => {
    return (
      <View>
        {spellElement != null && (
          <View style={styles.elementContainer}>
            <Markdown>{t("Element:")}</Markdown>
            <SpellElementTag element={spellElement} />
          </View>
        )}
      </View>
    );
  }, [spellElement, theme]);

  const mainExp = spell.getMainExp(state);

  const expText = useMemo(
    () => (
      <View style={[Styles.mt4, Styles.flexRowSpaceBetween]}>
        <View style={Styles.flex}>
          <Markdown>{t("Exp gain:")}</Markdown>
        </View>
        <View style={[Styles.ml8]}>
          <Markdown style={{ textAlign: "right" }}>
            {mainExp.element != null
              ? t("+{{amount}} {{element}} exp", {
                  amount: formatNumber(mainExp.expMin),
                  element: translateElement(mainExp.element),
                })
              : t("+{{min}}~{{max}} exp (various)", {
                  min: formatNumber(mainExp.expMin),
                  max: formatNumber(mainExp.expMax),
                })}
          </Markdown>
        </View>
      </View>
    ),
    [mainExp.element, mainExp.expMin, mainExp.expMax],
  );

  const cooldownContainer = useMemo(() => {
    const cooldown = spell.getCooldown(state);
    const cooldownText = t("{{cooldown}} sec", {
      cooldown: formatNumber(cooldown, { showDecimals: true }),
    });
    return (
      <View>
        {cooldown > 0 && (
          <View style={styles.costContainer}>
            <Markdown>{t("Cooldown:")}</Markdown>
            <Markdown containerStyle={{ flex: null }}>{cooldownText}</Markdown>
          </View>
        )}
      </View>
    );
  }, [(!props.collapsed || Platform.OS === "web") && spell.getCooldown(state)]);

  const toggleFavorite = useCallback(() => {
    stateRef.current.apply((state) =>
      isInFavoriteSpells(state, spell)
        ? removeFromFavoriteSpells(state, spell)
        : addFavoriteSpell(state, spell),
    );
  }, []);

  const favoritesButton = useMemo(
    () => (
      <Ionicons
        accessibilityLabel={
          isInFavoriteSpells(state, spell)
            ? t("Remove from Favorites")
            : t("Add to Favorites")
        }
        onPress={toggleFavorite}
        name={isInFavoriteSpells(state, spell) ? "heart" : "heart-outline"}
        color={theme.colors.onBackground}
        size={32}
      />
    ),
    [
      !props.showAddToQuickbar && isInFavoriteSpells(state, spell),
      toggleFavorite,
      theme,
    ],
  );

  const elementTheme = state.options.colorSpellButtonsByElement
    ? getThemeByElementAndDarkMode(spell.getElement(), theme.dark)
    : theme;

  const disabled = !spell.isEnabled(state);
  const buttonOnPress = useCallback(() => {
    if (props.onPress != null) {
      props.onPress(spell);
    } else {
      stateRef.current.apply(
        spell.doAction.bind(spell, { isAutomatic: false }),
      );
    }
  }, [props.onPress, spell]);
  const auxOptions = useAuxOptions();
  const buttonContents = useMemo(
    () => <SpellViewButtonContents spell={spell} theme={elementTheme} />,
    [spell, elementTheme],
  );
  const button = useMemo(
    () => (
      <TooltipWrapper
        tooltip={
          props.collapsed || props.isMinimal ? (
            <View>
              {descriptionContainer}
              {elementText}
              {expText}
              {cooldownContainer}
            </View>
          ) : undefined
        }
        tooltipPosition={
          showAddToQuickbar
            ? TooltipPositionType.RIGHT_OF_ELEMENT
            : TooltipPositionType.LEFT_OF_ELEMENT
        }
        tooltipId={
          spell.getId() +
          (showAddToQuickbar ? "yes" : "no") +
          props.tooltipExtra
        }
      >
        <Button
          key={spell.getId()}
          compact={true}
          enableAutoRepress={true}
          onPress={buttonOnPress}
          style={Styles.flex}
          labelStyle={[
            styles.buttonLabelStyle,
            auxOptions.compactLayout && { height: 36 },
          ]}
          contentStyle={[
            styles.buttonContentStyle,
            auxOptions.compactLayout && { height: 36 },
          ]}
          theme={elementTheme}
          // @ts-ignore I added this myself
          TextComponent={View}
          disabled={disabled}
          disableDisabledRenderingOptimization={true}
        >
          {buttonContents}
        </Button>
      </TooltipWrapper>
    ),
    [
      spell.getDisplayName(state),
      buttonOnPress,
      disabled,
      descriptionContainer,
      elementText,
      expText,
      cooldownContainer,
      props.collapsed,
      props.isMinimal,
      showAddToQuickbar,
      props.tooltipExtra,
      buttonContents,
      elementTheme,
      auxOptions.compactLayout,
    ],
  );

  const addToQuickbarButton = useMemo(
    () => (
      <Button
        onPress={() =>
          stateRef.current.apply((state) => addActionToQuickbar(state, spell))
        }
        style={styles.quickbarButtonContainer}
        disabled={
          isInQuickbar(state, {
            type: "action",
            id: spell.getId(),
          }) || isQuickbarFull(state)
        }
      >
        {t("Add to Quickbar")}
      </Button>
    ),
    [
      props.showAddToQuickbar &&
        isInQuickbar(state, {
          type: "action",
          id: spell.getId(),
        }),
      props.showAddToQuickbar && isQuickbarFull(state),
    ],
  );

  const isSpellPermaCastAllowed =
    spell.isPermaCastAllowed() && hasFlag(state, Flag.PermaCastUnlocked);
  const calculatePermaCastStuff = isSpellPermaCastAllowed && !props.collapsed;
  const permaCastManaCost = calculatePermaCastStuff
    ? spell.getPermaCastManaCostPerSec(state)
    : 0;
  const permaCastDelay = calculatePermaCastStuff
    ? spell.getPermaCastDelaySec(state)
    : 0;
  const permaCastWizardPowerNeededForSpell = calculatePermaCastStuff
    ? permaCastWizardPowerNeeded(state, spell)
    : 0;
  const permaCastWizardsNeededForSpell = calculatePermaCastStuff
    ? permaCastWizardsNeeded(state, spell)
    : 0;
  const isInPermaCast = isSpellInPermaCast(state, spell);

  const hasEnoughMaxManaForPermacast = calculatePermaCastStuff
    ? spell.getManaCost(state) <= getResourceCap(state, Resource.Mana)
    : true;

  const permaCastButton = useMemo(
    () =>
      calculatePermaCastStuff ? (
        <Button
          onPress={() =>
            stateRef.current.apply((state) =>
              isSpellInPermaCast(state, spell)
                ? removeSpellFromPermaCast(state, spell)
                : addSpellToPermaCast(state, spell),
            )
          }
          disabled={!hasEnoughMaxManaForPermacast && !isInPermaCast}
          compact={true}
          mode="outlined"
        >
          {!isInPermaCast ? t("Perma-Cast") : t("Stop Perma-Cast")}
        </Button>
      ) : null,
    [
      calculatePermaCastStuff,
      spell,
      hasEnoughMaxManaForPermacast,
      isInPermaCast,
      permaCastManaCost,
      permaCastWizardsNeededForSpell,
    ],
  );

  const isEmpowerable = spell.isEmpoweringPossible(state);
  const isEmpowered = spell.isEmpowered(state);
  const empowerButton = useMemo(
    () =>
      isEmpowerable ? (
        <Button
          onPress={() =>
            stateRef.current.apply((state) => toggleEmpowered(state, spell))
          }
          compact={true}
          mode="outlined"
        >
          {!isEmpowered ? t("Empower") : t("Cancel Empower")}
        </Button>
      ) : null,
    [spell, isEmpowerable, isEmpowered],
  );

  const extraActionsRow = useMemo(
    () => (
      <View
        style={[
          Styles.mt8,
          Styles.mb4,
          Styles.alignSelfNormal,
          Styles.flexRowAlignCenter,
          { gap: 8 },
        ]}
      >
        {empowerButton}
        {permaCastButton}
      </View>
    ),
    [permaCastButton, empowerButton],
  );

  const permaCastParams = calculatePermaCastStuff
    ? {
        sec: formatNumber(permaCastDelay, { showDecimals: true }),
        mana: formatNumber(permaCastManaCost, { showDecimals: true }),
        wizardPower: formatNumber(permaCastWizardPowerNeededForSpell, {
          showDecimals: true,
        }),
        wizText:
          permaCastWizardsNeededForSpell > 1
            ? t("{{wiz}} wizards", {
                wiz: formatNumber(permaCastWizardsNeededForSpell),
              })
            : t("{{wiz}} wizard", {
                wiz: formatNumber(permaCastWizardsNeededForSpell),
              }),
      }
    : {};

  const permaCastText = calculatePermaCastStuff
    ? isInPermaCast
      ? hasEnoughMaxManaForPermacast
        ? t(
            "You are Perma-Casting this spell every {{sec}} sec. Requires {{mana}}:mana:/sec and {{wizardPower}}:wizardpower:/sec (about {{wizText}})",
            permaCastParams,
          )
        : t(
            "You are Perma-Casting this spell, but your Max:mana: is not enough, so it will not get cast.",
          )
      : hasEnoughMaxManaForPermacast
      ? t(
          "You may Perma-Cast this spell to automatically cast it every {{sec}} sec. Requires {{mana}}:mana:/sec and {{wizardPower}}:wizardpower:/sec (about {{wizText}})",
          permaCastParams,
        )
      : t("You may Perma-Cast this spell, but your Max:mana: is not enough.")
    : "";

  const empowerText = isEmpowerable
    ? isEmpowered
      ? t("You are Empowering this spell.")
      : t(
          "You may Empower this spell to increase its effects at a higher :mana: cost.",
        )
    : "";

  const extrasText =
    calculatePermaCastStuff && isEmpowerable
      ? t("{{empowerText}}\n\n{{permaCastText}}", {
          permaCastText,
          empowerText,
        })
      : calculatePermaCastStuff
      ? permaCastText
      : empowerText;

  const extrasExplanation = useMemo(
    () => <Markdown>{extrasText}</Markdown>,
    [extrasText],
  );

  return useMemo(() => {
    if (props.isMinimal) {
      return <View style={styles.buttonContainerView}>{button}</View>;
    }
    return (
      <View
        style={[styles.actionContainer, auxOptions.compactLayout && Styles.mv4]}
        key={spell.getId()}
      >
        <View style={styles.buttonRowContainer}>
          <View style={styles.buttonContainerView}>{button}</View>
          <TouchableWithoutFeedback
            accessibilityLabel={props.collapsed ? t("Expand") : t("Collapse")}
            onPress={props.toggleCollapsed}
          >
            <View style={styles.expandContainer}>
              <Entypo
                name={props.collapsed ? "chevron-right" : "chevron-down"}
                size={24}
                color={theme.colors.onBackground}
              />
            </View>
          </TouchableWithoutFeedback>
        </View>
        {(!props.collapsed || !props.showAddToQuickbar) && (
          <GameStateFreezer isVisible={!props.collapsed}>
            {/* There is some weirdness on Android where even with display: "none" some piece is displayed */}
            <View style={props.collapsed && { display: "none", opacity: 0 }}>
              <View style={styles.descriptionRow}>
                {descriptionContainer}
                {!showAddToQuickbar && favoritesButton}
                {showAddToQuickbar && addToQuickbarButton}
              </View>
              {elementText}
              {expText}
              {cooldownContainer}
              {props.showWizards &&
              !isInPermaCast &&
              isAutocastUnlocked(state) ? (
                <View style={Styles.mt4}>
                  <SpellWizardsInfo spellId={spell.getId()} />
                </View>
              ) : null}
              {(calculatePermaCastStuff || isEmpowerable) &&
                !showAddToQuickbar &&
                extraActionsRow}
              {extrasText != "" && !showAddToQuickbar
                ? extrasExplanation
                : null}
            </View>
          </GameStateFreezer>
        )}
      </View>
    );
  }, [
    spell.getId(),
    auxOptions.compactLayout,
    !props.collapsed && cooldownContainer,
    !props.collapsed && expText,
    !props.collapsed && descriptionContainer,
    showAddToQuickbar,
    showAddToQuickbar && addToQuickbarButton,
    showAddToQuickbar && state.run.quickbar.length,
    props.collapsed,
    props.showWizards,
    isInPermaCast,
    isAutocastUnlocked(state),
    theme,
    toggleFavorite,
    props.isMinimal,
    props.toggleCollapsed,
    favoritesButton,
    button,
    calculatePermaCastStuff,
    isEmpowerable,
    permaCastButton,
    extrasExplanation,
  ]);
}

export function SpellView(props: SpellViewProps & { isVisible?: boolean }) {
  const state = useGameState();

  return useMemo(
    () => <SpellViewMemoized state={state} {...props} />,
    [
      props.spell.getId(),
      props.collapsed,
      props.tooltipExtra,
      props.showAddToQuickbar,
      props.toggleCollapsed,
      props.isVisible !== false ? state : false,
    ],
  );
}

