import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { ItemStack } from "../../backend/items/Item";
import { Items } from "../../backend/items/Items";
import { PouchItem } from "../../backend/items/equipment/PouchItem";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import { Markdown } from "../utility/Markdown";
import { DescriptionViewer, EffectViewer } from "../utility/Miscellaneous";
import { Text } from "../utility/Text";
import { useGameState } from "../utility/useGameState";
import { ItemTags } from "./ItemTags";
import { ItemTile } from "./ItemTile";

export interface ItemInfoProps {
  itemStack: ItemStack;
  showTile: boolean;
}

export function ItemInfo(props: ItemInfoProps) {
  const state = useGameState();

  const { itemId, params } = props.itemStack.itemOccurrence;
  const item = Items.getById(itemId);
  const name = item.getName(params);
  const description = item.getDescription(state, params);
  const effect = item.getEffect(state, params);
  const tags = item.getTags();
  const usesPerCombat =
    item instanceof PouchItem ? item.getUsesPerCombat(state, params) : null;
  const usesPerCombatExplanation =
    item instanceof PouchItem
      ? item.getUsesPerCombatExplanation(state, params)
      : null;

  return useMemo(
    () => (
      <View style={styles.itemDetailsContainer}>
        <View style={styles.main}>
          <Text h3>
            {name}
            {props.itemStack.amount > 1 && ` (${props.itemStack.amount})`}
          </Text>
          <View style={styles.tagRow}>
            <ItemTags tags={tags} />
          </View>
          {description && <DescriptionViewer>{description}</DescriptionViewer>}
          {effect && <EffectViewer>{effect}</EffectViewer>}
          {usesPerCombat && usesPerCombat > 1 && (
            <Markdown>
              {t("Can be used ^{{times}}^<{{explanation}}> times per combat", {
                times: formatNumber(usesPerCombat),
                explanation: usesPerCombatExplanation,
              })}
            </Markdown>
          )}
        </View>
        {props.showTile && (
          <ItemTile
            title={name}
            icon={item.getId()}
            quality={params?.itemQuality}
            tooltipIdSuffix="itemInfo"
          />
        )}
      </View>
    ),
    [
      name,
      props.itemStack.amount,
      JSON.stringify(tags),
      description,
      effect,
      usesPerCombat,
      params?.itemQuality,
      item.getId(),
    ],
  );
}

const styles = StyleSheet.create({
  itemDetailsContainer: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-start",
  },
  main: {
    flex: 1,
  },
  tagRow: {
    marginVertical: 4,
  },
});
