import {
  FamiliarCompanionStatusType,
  getCurrentCompanionData,
} from "../familiars/Familiar";
import { GameState } from "../GameState";

export function hasCurrentActiveCompanion(state: GameState): boolean {
  return (
    getCurrentCompanionData(state) != null &&
    getCurrentCompanionData(state).status.companionStatus ===
      FamiliarCompanionStatusType.Combat
  );
}

export function hasCurrentCompanion(state: GameState): boolean {
  return getCurrentCompanionData(state) != null;
}
