import React, { useMemo } from "react";
import { StyleSheet, View } from "react-native";
import { useTheme } from "react-native-paper";
import {
  Resource,
  getResourceAmount,
  getResourceCap,
  getResourceInfo,
  getSortedResourceKeys,
  translateResource,
} from "../../backend/Resources";
import {
  calculateIncomePerSecond,
  explainIncomePerSecText,
} from "../../backend/timetick/IncomeOverTime";
import { formatNumber } from "../../utils/FormattingUtils";
import { t } from "../../utils/I18n";
import Styles from "./Styles";
import { Text } from "./Text";
import { GameIcon } from "./icons/GameIcon";
import Tooltip from "./tooltip/PaperTooltip";
import { useGameState } from "./useGameState";

const EPSILON = 1e-6;

function ResourceRow(props: {
  resource: Resource;
  current: number;
  cap: number;
  income: number;
  hideIncome: boolean;
  explanation: string;
}) {
  const resourceInfo = getResourceInfo(props.resource);
  const isCapped = props.current >= props.cap - EPSILON;
  const theme = useTheme();
  const incomeComponent = useMemo(() => {
    if (props.hideIncome) {
      return null;
    }
    if (props.explanation != "") {
      return (
        <View style={styles.income}>
          {props.income != 0 && (
            <Tooltip title={props.explanation}>
              <Text
                style={[
                  props.income < 0 && { color: theme.colors.error },
                  Styles.underlined,
                ]}
                doNotScaleFonts={true}
              >
                {t("{{incomeSign}}{{income}}/s", {
                  incomeSign: props.income > 0 ? "+" : "-",
                  income: formatNumber(Math.abs(props.income), {
                    showDecimals: true,
                  }),
                })}
              </Text>
            </Tooltip>
          )}
        </View>
      );
    }
    return (
      <View style={styles.income}>
        {props.income != 0 && (
          <Text
            style={props.income < 0 && { color: theme.colors.error }}
            doNotScaleFonts={true}
          >
            {t("{{incomeSign}}{{income}}/s", {
              incomeSign: props.income > 0 ? "+" : "-",
              income: formatNumber(Math.abs(props.income), {
                showDecimals: true,
              }),
            })}
          </Text>
        )}
      </View>
    );
  }, [props.income, props.hideIncome, props.explanation, theme]);

  const firstComponent = useMemo(
    () => (
      <View style={styles.inlineRow}>
        <GameIcon
          icon={resourceInfo.icon}
          style={styles.icon}
          small
          accessibilityLabel=""
        />
        <Text doNotScaleFonts={true}>
          {t("{{resource}}: ", { resource: translateResource(props.resource) })}
        </Text>
      </View>
    ),
    [props.resource, resourceInfo.icon],
  );

  return (
    <View style={styles.row} accessible={true}>
      {firstComponent}
      <View style={styles.inlineRow}>
        <Text
          style={isCapped && { color: theme.colors.secondary }}
          doNotScaleFonts={true}
        >
          {t("{{current}}/{{cap}}", {
            current: formatNumber(props.current, { showDecimals: true }),
            cap: formatNumber(props.cap),
          })}
        </Text>
        {incomeComponent}
      </View>
    </View>
  );
}

const ResourceRowMemo = React.memo(ResourceRow);

export function ResourceViewer(props: { isVisible?: boolean }) {
  const state = useGameState();
  const resourceKeys = getSortedResourceKeys(state) as Array<Resource>;
  const income = calculateIncomePerSecond(state);
  const incomeExplanation = explainIncomePerSecText(state);
  const hasAnyIncome = Object.keys(income).some(
    (resource) => income[resource as Resource] != 0,
  );
  const rows = useMemo(
    () =>
      resourceKeys.map((resourceName) => {
        return (
          <ResourceRowMemo
            resource={resourceName}
            current={getResourceAmount(state, resourceName)}
            cap={getResourceCap(state, resourceName)}
            income={income?.[resourceName] ?? 0}
            hideIncome={!hasAnyIncome}
            explanation={incomeExplanation?.[resourceName] ?? ""}
            key={resourceName}
          />
        );
      }),
    [props.isVisible !== false ? state : false],
  );
  return <View style={styles.container}>{rows}</View>;
}

const styles = StyleSheet.create({
  container: {
    justifyContent: "center",
    alignItems: "stretch",
  },
  row: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "flex-end",
  },
  inlineRow: {
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
  },
  icon: {
    marginRight: 2,
  },
  income: {
    width: 65,
    marginLeft: 8,
  },
});
