import memoize from "sonic-memoize";
import { t } from "../../../utils/I18n";
import { cacheBooleanTrueOnly, cacheByTickCount } from "../../Caching";
import { GameState, GameStateTransform } from "../../GameState";
import {
  Familiars,
  getFamiliarData,
  pushToFamiliarMessageLog,
} from "../../familiars/Familiar";
import { Identifiable } from "../../generic/Identifiable";
import { applyTransformations } from "../../transformation/Transformation";
import { TransformationTags } from "../../transformation/TransformationTags";
import { getSuccessfulExplorationsForFloor, startCombat } from "../Exploration";
import { Enemy } from "../enemies/Enemy";
import { isDungeonFloorCompletedThisRun } from "./Dungeons";

export interface ExplorationPossibility {
  weight: number;
  transforms: ExplorationOutcome;
}

export interface ExplorationOutcome {
  enemy?: Enemy;
  normal: GameStateTransform;
  familiar: (state: GameState, familiarId: string) => GameState;
}

export function encounterEnemy(enemy: Enemy): ExplorationOutcome {
  return {
    enemy: enemy,
    normal: startCombat(enemy),
    familiar: (state, familiarId) => {
      // Determine success rate
      const familiarData = getFamiliarData(state, familiarId);
      const familiar = Familiars.getById(familiarData.familiarClassId);
      const familiarLevel = familiar.getLevel(state, familiarId);
      const enemyLevel = enemy.getLevel();
      let successRate = 1.0;
      if (familiarLevel < enemyLevel) {
        successRate = Math.pow(0.5, enemyLevel - familiarLevel);
      }
      if (Math.random() < successRate) {
        state = pushToFamiliarMessageLog(
          state,
          familiarId,
          t(`Your familiar defeated {{enemy}}!`, { enemy: enemy.getName() }),
        );
        state = enemy.onDeath(state, true, familiarId);
      } else {
        state = pushToFamiliarMessageLog(
          state,
          familiarId,
          t(`Your familiar faced {{enemy}}, but was defeated!`, {
            enemy: enemy.getName(),
          }),
        );
      }
      return state;
    },
  };
}

const explorationBossRequirementTags = [
  TransformationTags.ExplorationBossRequirement,
];

export abstract class DungeonFloor implements Identifiable {
  abstract getId(): string;
  abstract getFloorName(): string;

  getIdForTallyingClears(): string {
    return this.getId();
  }

  getExplorationRequiredTimeSecBase(): number {
    return 5.0;
  }

  _getExplorationSuccessesForBossBase(): number {
    return 50;
  }

  getExplorationSuccessesForBoss(state: GameState): number {
    return Math.max(
      Math.ceil(
        applyTransformations(
          explorationBossRequirementTags,
          state,
          this._getExplorationSuccessesForBossBase(),
          { floor: this },
        ),
      ),
      1,
    );
  }

  doExplore(
    state: GameState,
    isFromFamiliars: boolean,
    forcedRoll?: number | undefined,
  ): ExplorationOutcome | null {
    const possibilities = cacheByTickCount(
      state,
      this.getId() + "ExplorationPossibilities" + isFromFamiliars,
      (state) => {
        const possibilities = this.getExplorationPossibilities(
          state,
          isFromFamiliars,
        );
        return possibilities;
      },
    );
    const totalWeight = possibilities.reduce(
      (sumToNow, possibility) => sumToNow + possibility.weight,
      0,
    );
    let explorationRoll = (forcedRoll ?? Math.random()) * totalWeight;
    for (let idx in possibilities) {
      const possibility = possibilities[idx];
      const weight = possibility.weight;
      explorationRoll -= weight;
      if (explorationRoll < 0) {
        return possibility.transforms;
      }
    }
    return null;
  }

  abstract getBoss(state: GameState): Enemy | undefined;

  abstract getBreedingLevel(): number;

  doFightBoss(state: GameState): GameStateTransform | undefined {
    const boss = this.getBoss(state);
    if (boss) {
      return startCombat(boss);
    }
  }

  canFightBoss(state: GameState): boolean {
    return cacheBooleanTrueOnly(
      state,
      "dungeonFloorFightBoss." + this.getId(),
      (state) => {
        const couldFightBoss =
          (getSuccessfulExplorationsForFloor(state, this.getId()) >=
            this.getExplorationSuccessesForBoss(state) ||
            isDungeonFloorCompletedThisRun(state, this)) &&
          this.getBoss(state) != undefined;
        return couldFightBoss;
      },
    );
  }

  isUnlocked(state: GameState): boolean {
    return false;
  }

  getNextFloor(state: GameState): DungeonFloor | undefined {
    return undefined;
  }

  abstract isMainStoryDungeonFloor(): boolean;

  getMinimumMPLRecommendation(): number | undefined {
    return undefined;
  }

  getMaximumMPLRecommendation(): number | undefined {
    return undefined;
  }

  abstract getBaseExplorationPossibilities(
    state: GameState,
  ): ExplorationPossibility[];

  getExplorationPossibilities(
    state: GameState,
    isFromFamiliars: boolean,
  ): ExplorationPossibility[] {
    const basePossibilities = this.getBaseExplorationPossibilities(state);
    return basePossibilities.map((possibility) => ({
      weight: applyTransformations(
        getExplorationPossibilityTags(
          this.getId(),
          possibility.transforms.enemy?.getId(),
          isFromFamiliars,
        ),
        state,
        possibility.weight,
      ),
      transforms: possibility.transforms,
    }));
  }

  isDangerousForFamiliar(state: GameState, familiarId: string): boolean {
    const familiarData = getFamiliarData(state, familiarId);
    const familiar = Familiars.getById(familiarData.familiarClassId);
    const familiarLevel = familiar.getLevel(state, familiarId);
    const explorationPossibilities = this.getExplorationPossibilities(
      state,
      true,
    );

    let succeedWeight = 0,
      failWeight = 0;
    for (let possibility of explorationPossibilities) {
      const weight = possibility.weight;
      const enemy = possibility.transforms.enemy;
      const enemyLevel = enemy.getLevel();
      let successRate = 1.0;
      if (familiarLevel < enemyLevel) {
        successRate = Math.pow(0.5, enemyLevel - familiarLevel);
      }
      succeedWeight += weight * successRate;
      failWeight += weight * (1 - successRate);
    }

    const succeedPercent = succeedWeight / (succeedWeight + failWeight);
    return succeedPercent < 0.8;
  }

  calculateAverageEnemyLevelForFamiliars(state: GameState): number {
    const explorationPossibilities = this.getExplorationPossibilities(
      state,
      true,
    );

    let totalWeight = 0,
      levelSum = 0;

    for (let possibility of explorationPossibilities) {
      const weight = possibility.weight;
      const enemy = possibility.transforms.enemy;
      const enemyLevel = enemy.getLevel();
      totalWeight += weight;
      levelSum += enemyLevel * weight;
    }

    return levelSum / totalWeight;
  }
}

const getExplorationPossibilityTags = memoize(
  (floorId: string, enemyId: string | undefined, isFromFamiliars: boolean) => [
    floorId,
    enemyId,
    TransformationTags.ExplorationWeight,
    isFromFamiliars
      ? TransformationTags.FromFamiliarExploration
      : TransformationTags.FromExploration,
  ],
);
